import angular from 'angular'
import { AUTH_ACTIONS } from '@app/angular/constants/authActions'
import template from './loyaltyPlacement.html'

class LoyaltyPlacement {
  constructor($filter, lang, registrationService, userService) {
    Object.assign(this, {
      $filter,
      lang,
      registrationService,
      userService,
    })
  }

  $onInit() {
    this.title = this.title || this.lang.values.loyaltySavings
    this.subtitle = this.subtitle || this.lang.values.loyaltySubtitle
    this.ctaButtonLabel = this.ctaButtonLabel || this.lang.values.loyaltyJoinNow
    this.ctaButtonAction =
      this.ctaButtonAction ||
      (() =>
        this.registrationService
          .open(AUTH_ACTIONS.LOYALTY)
          .then(() => this.userService.fetchLoyaltyCards()))
  }

  get formattedTitle() {
    return this.lang.template(this.title, {
      savings: this.savings > 0 ? this.$filter('currency')(this.savings) : '',
    })
  }
}

export default angular.module('LoyaltyPlacementModule', []).component('loyaltyPlacement', {
  bindings: {
    savings: '<',
    title: '@?',
    subtitle: '@?',
    ctaButtonLabel: '@?',
    ctaButtonAction: '&?',
  },
  template,
  controller: LoyaltyPlacement,
})
