/* eslint-disable max-lines */

import _ from 'lodash'
import angular from 'angular'
import { captureException } from '@sentry/browser'

import {
  cart as enterpriseCartService,
  list as enterpriseListService,
  product as enterpriseProductService,
  shoppingList as shoppingListService,
} from '@instacart/enterprise-services'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import { CART_TYPES } from '@app/angular/constants/cart'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import {
  ANALYTICS,
  EVENTS,
  SOURCES,
} from '@app/angular/services/modules/analytics/analyticsConstants'
import alcoholValidationErrorModalTemplate from '@app/angular/services/templates/alcoholValidation/alcoholValidationErrorModal.html'
import { isRxProduct } from '@instacart/enterprise-services/product/utils'
import {
  getRxItemsList,
  getItemDisplayName,
} from '@instacart/enterprise-services/shopping-list/_common/utils'
import { getProductFulfillmentRetailer } from '@app/utils/banners'
import { obfuscationHelper } from '@app/services/rx/utils'
import InvalidCartItemsDisplayTemplate from '../../components/shared/invalidCartItemsDisplay/invalidCartItemsDisplay.html'
import InvalidCartItemsDisplayControllerModule from '../../components/shared/invalidCartItemsDisplay/invalidCartItemsDisplay.controller'
import CatalogCategoriesServiceComponentModule from '../../components/shop/services/catalogCategoriesService'
import RegistrationServiceModule from '../../components/registration/registrationService'
import PolicyServiceModule from '../../components/policy/policyService'
import IwsRegulationAlertModalServiceModule from '../../components/iwsRegulationAlertModal/iwsRegulationAlertModalService'

export default angular
  .module('cartServiceModule', [
    InvalidCartItemsDisplayControllerModule.name,
    CatalogCategoriesServiceComponentModule.name,
    RegistrationServiceModule.name,
    PolicyServiceModule.name,
    IwsRegulationAlertModalServiceModule.name,
  ])
  .service(
    'cartService',
    function cartService(
      $q,
      $rootScope,
      $state,
      $uibModal,
      $uibModalStack,
      $window,
      Restangular,
      analytics,
      analyticsHelper,
      auth,
      catalogCategoriesService,
      config,
      configurableProductService,
      eComLightService,
      ebtCardService,
      errorModalService,
      instacartCustomerLinkModalService,
      iwsRegulationAlertModalService,
      lang,
      localStorageService,
      policyService,
      productDetailsModal,
      rateApp,
      registrationService,
      reservationService,
      rxUpSellService,
      shoppingListCache,
      shoppingSelectorService,
      unataCommService
    ) {
      const service = this
      let endpoint
      service.currentEnterpriseService = null

      service.TIMESLOT_UNAVAILABLE_ERROR = new Error('The selected timeslot is no longer available')

      /**
       * Creates an object that will hold cart items grouped by cart type:
       *
       * {
       *   grocery: [],
       *   catering: [],
       *   ...
       * }
       */
      service.itemsGroupedByCartType = Object.values(CART_TYPES).reduce((obj, cartType) => {
        obj[cartType] = []
        return obj
      }, {})

      /**
       * This attribute was created to hold the cart total returned by the
       * cart validate call. It's currently being used by the *getCartFinalTotal* method
       */
      service.cartFinalTotal = null

      service.finalTaxTotal = null

      service.updateMessage = null

      service.isSubstitutionNotesOpen = false

      service.obfuscatedRxItems = {}

      // Map cart types to category strings for displaying items based on cart type.
      service.cartTypeToCategoryMapping = {
        [CART_TYPES.GROCERY]: [],
        [CART_TYPES.CATERING]: [],
      }

      service.isInstacartOrder = cart => {
        return Boolean(cart.order?.ext_data?.instacart_order_url)
      }

      // Listening for every time the user context intent changes and changing the API endpoint if the user switches between instore list mode
      // and not instore list mode (cart mode). Changing endpoint between cart and user-list.
      const updatePreviewTimeslots = notifyOnError => {
        const cartId = service.getCartId(service.getCurrentCartType())
        reservationService.updateTimeslotPreview(cartId, notifyOnError)
      }

      $rootScope.$on('auth', (event, action, user) => {
        if (action === 'userUpdated' && !auth.isLoggingInDuringCheckout) {
          const oldEndpoint = endpoint

          if (user && user.context && user.context.intent === SHOPPING_MODES.INSTORE_LIST) {
            endpoint = '/user_lists/default'
            service.currentEnterpriseService = enterpriseListService
          } else {
            endpoint = '/cart'
            service.currentEnterpriseService = enterpriseCartService
          }

          // If endpoint has changed, fetch the cart.
          if ((oldEndpoint !== endpoint && oldEndpoint !== undefined) || !service.cart) {
            service.fetch().then(() => updatePreviewTimeslots(false))
          }
        } else if (action === 'userLoggedIn') {
          service
            .fetch()
            .then(() => updatePreviewTimeslots(false))
            .then(() => {
              // check for if rx upsell should be open on redirected sso cart call
              if (rxUpSellService.rxSSO && service.cartHasRxItem()) {
                analytics.logSegmentEvent('rx.upsell_user_loggedin', {
                  hasRxItem: service.cartHasRxItem(),
                  fromSSO: rxUpSellService.rxSSO,
                })

                rxUpSellService.open(
                  service.removeRxItems,
                  service.fetch,
                  getRxItemsList(service.cart?.items)[0].rx_deliverable_context
                )
              }
            })
        } else if (action === 'contextUpdated') {
          updatePreviewTimeslots(false)
        }
      })

      // Provide a more efficient way to watch for changes
      // to the cart elsewhere in the app
      service.updateTimestamp = function () {
        service.lastUpdated = new Date().getTime()
      }

      // Saves cart into service and updates timestamp
      const updateCart = newCart => {
        service.cart = newCart
        service.updateTimestamp()
        service.sendUnataCommCartMessage()

        service.getCategorizedProductsAndCategories().then(categorizedProductsAndCategories => {
          shoppingListCache.setCache({
            cart: service.cart,
            categorizedProductsAndCategories,
          })
        })
      }

      // Checks if trial cart intent changed after logging in
      // and shows the shopping context selector if so.
      service.showSelectorIfTrialCartIntentChanged = () => {
        const trialCartIntent = localStorageService.deprecatedGet('trialCartIntent')

        if (auth.user.status === 'trial' || trialCartIntent === null) {
          return
        }

        const currentIntent = auth.getCurrentShoppingMode() || auth.getContext().intent
        const userCartIntent = localStorageService.deprecatedGet('userCartIntent')

        if (currentIntent !== trialCartIntent || userCartIntent !== trialCartIntent) {
          shoppingSelectorService.open('shoppingContext')
        }

        localStorageService.deprecatedRemove('userCartIntent')
        localStorageService.deprecatedRemove('trialCartIntent')
      }

      // Fetches trialCartInstance if not in a trial session and logs both the
      // trial and current cart identifiers.
      service.logCartMergeAnalytics = function (cart) {
        const trialCartInstance = localStorageService.deprecatedGet('trialCartInstance')
        if (auth.user.status === 'trial' || trialCartInstance == null) {
          return
        }

        const defaults = {
          [SOURCES.SUCCESS]: true,
          [SOURCES.USER]: auth.user || null,
          [SOURCES.CART]: cart,
        }

        const helperPayload = analyticsHelper.createPayload(defaults, [ANALYTICS.CART.ITEM_DETAILS])

        const payload = {
          source_cart_id: trialCartInstance,
          target_cart_id: cart.cart_instance_id,
          result_cart_items: helperPayload.item_details,
          success: helperPayload.success,
        }

        analytics.logSegmentEvent(EVENTS.CART.CART_MERGE, payload)
        localStorageService.deprecatedRemove('trialCartInstance')
      }

      // Retrieve the cart from the backend
      // Will avoid duplicate calls to the API by checking service.isFetching
      service.fetch = function () {
        if (!service.isFetching) {
          service.isFetching = true

          service.cartFetchPromise = service.currentEnterpriseService.actions
            .getShoppingList()
            .then(cart => {
              service.showSelectorIfTrialCartIntentChanged()

              // Log a cart merge event, if cart merge occurred.
              service.logCartMergeAnalytics(cart)

              // Initialize RX obfuscation here
              service.setObfuscatedRxItems(cart)

              updateCart(cart)
              $rootScope.modifyingOrder = Boolean(cart.order)
              $rootScope.modifyingInstacartOrder = Boolean(
                cart.order && service.isInstacartOrder(cart)
              )
              return cart
            })
            .finally(() => {
              service.isFetching = false
            })
        }

        return service.cartFetchPromise
      }

      // Get the offer_progress from the cart for a given offerID
      service.getOfferProgress = offerID =>
        _.find(service.cart && service.cart.offer_progress, { id: offerID })

      // Get all the offers from the cart with a type === 'cart'
      service.getAllCartTypeOffers = () =>
        _.filter(
          service.cart && service.cart.offer_progress,
          item => item.condition.type === 'cart'
        )

      /**
       * Returns a list of item ids that have offers from offer progress.
       * Memoized to be called repeatedly in views.
       *
       * @param {array} offerProgress - Offer progresses
       */
      service.getCartItemIdsFromOffers = _.memoize(offerProgress =>
        (offerProgress || []).reduce(
          (cartIds, offer) => [...Object.keys(offer.product_discounts || {}), ...cartIds],
          []
        )
      )

      /**
       * Checks whether an offer exists for a given item.
       *
       * @param {object} item - Cart item to check for offer
       */
      service.itemHasOffer = item =>
        service.getCartItemIdsFromOffers(service.cart?.offer_progress).includes(item.id)

      // Fetch a cart or user-list item for the given product from the redux state.
      service.getItem = (product, selectedConfiguration) =>
        service.currentEnterpriseService.selectors.getItem(
          product.store_product || product,
          selectedConfiguration
        )

      // Fetch all items in the cart or user-list.
      service.getItems = () => service.currentEnterpriseService.selectors.getItems()

      // Remove an item
      service.removeItem = function (product, selectedConfiguration, doNotSync) {
        // For prep_style configurable product, set selectedConfiguration to null
        // since they can be added from the product cell without a selected configuration.
        if (selectedConfiguration && !selectedConfiguration[0]) {
          selectedConfiguration = null
        }
        // Update the item quantity in redux state
        service.currentEnterpriseService.actions.updateItemQuantity(
          product.store_product || product,
          0,
          selectedConfiguration
        )
        // Update the angular cart object with updated item quantities.
        service.cart.items = service.getItems()
        const item = service.getItem(product.store_product || product, selectedConfiguration)
        let promise
        if (item) {
          if (doNotSync !== true) {
            service.updateTimestamp()
            promise = service.sync().then(() => {
              service.sendUnataCommCartMessage()
              $rootScope.$broadcast('cartService', 'remove', item)
            })
          } else {
            service.sendUnataCommCartMessage()
            $rootScope.$broadcast('cartService', 'remove', item)
          }

          return promise
        }
      }

      eventEmitter.on(EVENT_TYPES.REMOVE_ITEM_FROM_CART, (product, config) => {
        service.removeItem(product, config)
      })

      service.removeItems = products => {
        _.each(products, product => {
          service.removeItem(
            product,
            product.product_config && product.product_config.options,
            true
          )
        })
        const promise = service.sync()
        service.updateTimestamp()
        return promise
      }

      // Add one to the quantity of an item
      service.incrementItem = function (product, quantity, configuration) {
        let selectedConfiguration

        if (configuration) {
          selectedConfiguration = configuration.options
        }

        const updatedQuantity =
          service.getItem(product, selectedConfiguration).quantity + (quantity || 1)

        service.updateItemQuantity(product, updatedQuantity, selectedConfiguration)
      }

      // Subtract one from the quantity of an item
      service.decrementItem = function (product, quantity, configuration) {
        let selectedConfiguration

        if (configuration) {
          selectedConfiguration = configuration.options
        }

        const updatedQuantity =
          service.getItem(product, selectedConfiguration).quantity - (quantity || 1)

        return service.updateItemQuantity(product, updatedQuantity, selectedConfiguration)
      }

      // Update the quantity of an item
      service.updateItemQuantity = function (product, quantity, configuration) {
        if (quantity < product.ordering_parameters.min) {
          return {
            action: service.updateActions.remove,
            result: service.removeItem(product, configuration),
          }
        }
        return {
          action: service.updateActions.add,
          result: service.addItem(product, quantity, configuration),
        }
      }

      service.updateActions = {
        add: 'add',
        remove: 'remove',
      }

      eventEmitter.on(EVENT_TYPES.UPDATE_ITEM_QUANTITY, (product, quantity, config) => {
        service.updateItemQuantity(product, quantity, config)
      })

      // Add a new item to the cart or user-list.
      service.addItem = function (product, quantity, selectedConfiguration) {
        // For prep_style configurable product, set selectedConfiguration to null
        // since they can be added from the product cell without a selected configuration.
        if (
          selectedConfiguration &&
          selectedConfiguration.length > 0 &&
          !selectedConfiguration[0]
        ) {
          selectedConfiguration = null
        }
        // Update the item quantity in redux state
        service.currentEnterpriseService.actions.updateItemQuantity(
          product,
          quantity,
          selectedConfiguration
        )
        // Update the angular cart object with updated item quantities.
        service.cart.items = service.getItems()

        // If an iws product is added to cart, remember the initial time to show the iwsRegulationAlertModal. This modal should be shown once per cart for users
        // Note: using localStorage for loggedin users and sessionStorage for else
        if (
          config.iwsRegulatedStates?.includes(auth.user.store.address.province) &&
          !!getProductFulfillmentRetailer(
            product,
            auth.user?.store.store_banner?.ext_id,
            config.multipleBanners
          )
        ) {
          if (auth.user.status !== 'trial') {
            localStorageService.getItem('firstIWSAlcoholicProductAdded').then(hasShownModal => {
              if (!hasShownModal) iwsRegulationAlertModalService.open(product)
            })
          } else if (
            auth.user.status === 'trial' &&
            !$window.sessionStorage.getItem('firstIWSAlcoholicProductAdded')
          ) {
            iwsRegulationAlertModalService.open(product)
          }
        }

        // Updated item from the redux state
        const updatedItem = service.getItem(product.store_product || product, selectedConfiguration)
        service.updateTimestamp()
        const promiseResult = service.sync().then(() => {
          service.sendUnataCommCartMessage()
          // Broadcast the adding of this item to the nav cart controller so we can show the dropdown
          $rootScope.$broadcast('cartService', 'add', updatedItem, quantity)
        })

        return promiseResult
      }

      eventEmitter.on(EVENT_TYPES.ADD_ITEM_TO_CART, (product, quantity, config) => {
        service.addItem(product, quantity, config)
      })

      service.addItemConfigurable = function (product, quantity, selectedConfiguration) {
        service.addItem(product, quantity, selectedConfiguration)
        if ($uibModalStack.getTop()) {
          $uibModalStack.dismissAll()
        } else if ($rootScope.previousState && Boolean($rootScope.previousState.length)) {
          $state.go($rootScope.previousState)
        }
      }

      // Empty the cart by setting the quantity of all items to zero
      service.clear = function () {
        $rootScope.$broadcast('cartService', 'triggerClearModal')
        errorModalService
          .open('Are you sure you want to remove all items?', 'OK', 'Cancel', 'Confirm')
          .then(
            () => {
              $rootScope.$broadcast('cartService', 'clearConfirm')

              service.clearItems()
            },
            () => {
              $rootScope.$broadcast('cartService', 'clearCancel')
            }
          )
      }

      service.clearItems = () => {
        return service.currentEnterpriseService.actions
          .removeShoppingList()
          .then(updateCart)
          .then(service.sync)
          .then(() => service.sendUnataCommCartMessage())
      }

      // Delete checked items
      service.deleteCheckedItems = () => {
        $rootScope.$broadcast('cartService', 'triggerClearModal')
        errorModalService
          .open(
            lang.values.removeCheckedItemsConfirmation,
            lang.values.ok,
            lang.values.cancel,
            lang.values.confirm
          )
          .then(
            () => {
              $rootScope.$broadcast('cartService', 'clearConfirm')
              const itemsToRemove = service.cart.items.filter(item => item.checked)
              service.removeItems(itemsToRemove)
            },
            () => {
              $rootScope.$broadcast('cartService', 'clearCancel')
            }
          )
      }

      // Uncheck all items
      service.uncheckAllItems = () => {
        service.currentEnterpriseService.actions.uncheckAllItems()
        updateCart(service.currentEnterpriseService.selectors.getShoppingList())
        service.sync()
      }

      /**
       * Updates the context with fulfillment type and delivery address if needed
       * (isTemporaryModify would usually appear in the context, but in some environments it
       *  doesn't seem to stick, so we place it into local storage to be sure)
       *
       * @param { string } fulfillmentType
       * @param { object } deliveryAddress
       * @param { string } store_id
       * @param { string | null } [isTemporaryModify] - String of the route, exit modify when outside of route
       */
      const updateContext = (
        fulfillmentType,
        deliveryAddress,
        store_id,
        isTemporaryModify = null
      ) => {
        let payload = {
          intent: fulfillmentType,
          storeNumber: store_id,
        }

        if (fulfillmentType === SHOPPING_MODES.DELIVERY) {
          payload = { ...payload, ...auth.getContextFromAddress(deliveryAddress) }
        }

        localStorageService.deprecatedSet('isTemporaryModify', isTemporaryModify)

        return auth.setContext(payload)
      }

      /**
       * Updates the modify order status in the rootScope for a given *cart* object
       * @param {object} cart
       */
      const updateModifyOrderStatus = cart => {
        $rootScope.modifyingOrder = true
        if (service.isInstacartOrder(cart)) {
          $rootScope.modifyingInstacartOrder = true
        }
      }

      /**
       * Enters user into order modify mode
       *
       * @param {number} order - The order object
       * @param {boolean} [goToCart] - True to redirect to the cart, or false if you intend custom
       * @param {string | null} [isTemporaryModify] - String of the route, exit modify when outside of route
       *
       * Note: Generally goTOCart will be set to false when isTemporaryModify is set to true.
       *       These flags are used in a process to jump to a specific section in checkout for modify.
       */
      service.enterModifyState = (order, goToCart = true, isTemporaryModify = null) => {
        $rootScope.isAppLoading = true
        localStorageService.deprecatedSet('preModifyContext', auth.getContext())
        localStorageService.deprecatedSet('preModifyCartItems', service.cart.items)

        return Restangular.one('/cart')
          .one('modify_order')
          .customPOST({ order })
          .then(newCart => {
            const { fulfillment_type, delivery_address, store_id } = order

            return auth
              .update({ store_id })
              .then(() =>
                updateContext(fulfillment_type, delivery_address, store_id, isTemporaryModify)
              )
              .then(() => updateCart(newCart))
              .then(() => updateModifyOrderStatus(newCart))
              .then(reservationService.fetch)
              .then(() => goToCart && $state.go('main.checkout.cart'))
          })
          .catch(response => {
            if (response?.data?.error === HTTP_ERRORS.PII_UNAUTHORIZED) {
              // If PII has timed out
              registrationService.promptLogin(
                $state.current.name,
                $state.params,
                '',
                '',
                lang.values.loginToContinue
              )
            } else {
              errorModalService.open(response?.data?.message, 'OK')
            }
          })
          .finally(() => {
            $rootScope.isAppLoading = false
          })
      }

      // Modify an order
      service.modifyOrder = order => {
        errorModalService
          .open(lang.values.modifyCartAlert, 'OK', 'Cancel', 'Confirm')
          .then(() => service.enterModifyState(order))
      }

      /**
       * Cancel modifying an order
       *
       * @param {string} [postRoute]   - Optional: If specified, go to a custom route
       */
      service.cancelOrderModification = (postRoute = 'main.account.orderHistory.order') => {
        if (service.isCancellingOrderModification) return // Prevent double requests

        const orderId = this.cart?.order?.id

        // This no-id situation could occur if a cancel is requested after modification expired or
        // two redirects occur simultaneously (i.e. triggered by a modal etc)
        if (!orderId) {
          // If no longer modifying and if temporary modify flag is set:
          // Remove the flag to stop the triggering of repeated cancels
          // (isTemporaryModify would usually appear in the context, but in some environments it
          // doesn't seem to stick, so we place it into local storage to be sure)
          const context = auth.getContext()
          if (context.isTemporaryModify) {
            localStorageService.deprecatedSet('isTemporaryModify', null)
            return auth.setContext({ ...context, isTemporaryModify: null })
          }

          return $q.resolve()
        }

        $rootScope.isAppLoading = true
        service.isCancellingOrderModification = true

        return service
          .resetToPreModContext()
          .then(() => Restangular.one('/cart').one('modify_order').remove())
          .then(newCart => updateCart(newCart))
          .then(() => service.fetch())
          .then(() => reservationService.fetch())
          .then(() => service.resetToPreModCart())
          .then(() => {
            $rootScope.modifyingOrder = false
            $rootScope.modifyingInstacartOrder = false
            localStorageService.deprecatedSet('isTemporaryModify', null)

            return $state.go(postRoute, { id: orderId }, { reload: true })
          })
          .finally(() => {
            $rootScope.isAppLoading = false
            service.isCancellingOrderModification = false
          })
      }

      eventEmitter.on(EVENT_TYPES.CANCEL_ORDER_MODIFICATION, route => {
        service.cancelOrderModification(route)
      })

      /**
       * Put the cached cart items back into the cart and clear the cache.
       * Should only be run when there is a cached cart
       *
       */
      service.resetToPreModCart = () => {
        const preModifyCartItems = localStorageService.deprecatedGet('preModifyCartItems')
        if (preModifyCartItems) {
          return service
            .clearItems()
            .then(() => service.loadListToCart(preModifyCartItems))
            .finally(() => {
              localStorageService.deprecatedSet('preModifyCartItems', null)
            })
        }
        return Promise.resolve()
      }

      /**
       * Put the cached context back to the current context
       * Will try the context on the order if the cached copy is not there
       *
       */
      service.resetToPreModContext = () => {
        const preModifyContext = localStorageService.deprecatedGet('preModifyContext')
        const { delivery_address, fulfillment_type, store_id } = this.cart?.order || {}

        if (preModifyContext) {
          return updateContext(
            preModifyContext.intent,
            preModifyContext,
            preModifyContext.storeNumber,
            null
          ).finally(() => {
            localStorageService.deprecatedSet('preModifyContext', null)
          })
        }
        if (fulfillment_type && delivery_address && store_id) {
          return updateContext(fulfillment_type, delivery_address, store_id, null)
        }

        return Promise.resolve()
      }

      service.loadListToCart = function (list) {
        // list means this: [item1, item2], NOT our unata list object
        $rootScope.isAppLoading = true

        const invalidProducts = []

        _.each(list, (item = {}) => {
          const { actual_quantity, product_config, quantity, store_product } = item
          const options = product_config && product_config.options

          let configuration = null
          if (product_config) {
            configuration = configurableProductService.formatConfigForCart(store_product, options)
          }

          const existing = service.getItem(store_product, configuration)
          if (existing) {
            service.incrementItem(store_product, actual_quantity || quantity, product_config)
          } else if (!product_config && store_product.product_config) {
            invalidProducts.push(item)
          } else if (store_product.status !== false) {
            service.addItem(store_product, quantity || actual_quantity, options)
          }
        })

        // If there are any invalid products in the list, users will have to add them manually
        if (invalidProducts.length) {
          invalidProducts.forEach(product =>
            productDetailsModal.open(product.id, {
              message: {
                type: 'warn-light',
                content: lang.values.productMissingInformationAlertMessage,
              },
            })
          )
        }

        // The incrementItem & addItem calls above would have triggered a sync,
        //  we can use the promise returned to detect when that sync is completed
        return service
          .sync()
          .then(service.fetch)
          .finally(() => {
            $rootScope.isAppLoading = false
            // prompt for app rating
            if (
              auth.getContext().intent !== 'instore' &&
              config.mobileAppRate.callOnReOrderFromHistory
            ) {
              rateApp.promptAppReview()
            }
          })
      }

      service.isValidatingCart = null
      const resetValidationPromise = (result, optError) => {
        if (result) {
          service.isValidatingCart.resolve(result)
        } else {
          service.isValidatingCart.reject(optError || result)
        }
        service.isValidatingCart = null
      }

      const _handleTimeslotExpirationError = response => {
        errorModalService
          .open(response.data?.message || response.message, 'OK', undefined, undefined, true)
          .closed.then(() => resetValidationPromise(false, service.TIMESLOT_UNAVAILABLE_ERROR))
      }

      const _handleExternalFulfillmentProviderError = response => {
        $uibModal
          .open({
            resolve: {
              validateResponse: () => response,
            },
            controller: 'invalidCartItemsDisplayController',
            template: InvalidCartItemsDisplayTemplate,
            keyboard: false,
            backdrop: 'static',
          })
          .closed.then(() => resetValidationPromise(false))
      }

      const _handleExternalAuthError = payload => {
        instacartCustomerLinkModalService
          .openModal()
          .then(() => service.validateCart(payload))
          .catch(() => $state.go('main.checkout.cart'))
          .finally(() => resetValidationPromise(false))
      }

      const _handleAlcoholFulfillmentError = () => {
        errorModalService
          .openCustom(alcoholValidationErrorModalTemplate, { lang })
          .closed.then(() => resetValidationPromise(false))
      }

      const _handleGenericErrors = response => {
        errorModalService
          .open(response.data?.message || response.message, 'OK', undefined, undefined, true)
          .closed.then(() => resetValidationPromise(false))
      }

      const _handleCartValidateErrors = (response, payload) => {
        if (
          response.error === HTTP_ERRORS.EXT_FULFILLMENT_PROVIDER_ERROR &&
          response.unavailable_item_ids?.length
        ) {
          _handleExternalFulfillmentProviderError(response)
        } else if (
          response.error === HTTP_ERRORS.EXTERNAL_AUTH_ERROR &&
          config.instacart.enableAccountLinking
        ) {
          _handleExternalAuthError(payload)
        } else if (response.error === HTTP_ERRORS.ALCOHOL_FULFILLMENT_ERROR) {
          _handleAlcoholFulfillmentError(response)
        } else if (response.error === HTTP_ERRORS.TIMESLOT_EXPIRATION_ERROR) {
          _handleTimeslotExpirationError(response)
        } else {
          _handleGenericErrors(response)
        }
      }

      const _handleValidateSuccess = cart => {
        service.setCartFinalTotal(cart.total)
        service.setFinalTaxTotal(cart.tax_total)
        updateCart(cart)
        resetValidationPromise(true)
      }

      service.validateCart = (payload = null) => {
        if (service.isValidatingCart) {
          return service.isValidatingCart.promise
        }

        service.isValidatingCart = $q.defer()

        enterpriseCartService.promiseActions
          .validateCart(payload)
          .then(_handleValidateSuccess)
          .catch(response => _handleCartValidateErrors(response, payload))

        return service.isValidatingCart.promise
      }

      /**
       * Updates the cart to reflect changes to the local state, and then appropriately
       * updates the items in local state according to the data in the response
       *
       * @param {boolean} validate - wether it should trigger a cart validation after sync
       * @param {object} validatePayload - cart validation payload
       *
       * @returns {Promise} - a promise which resolves when the api request has finished.
       */
      service.sync = ({ validate = false, validatePayload } = {}) => {
        service.syncing = true
        service.updateTimestamp()
        return service.currentEnterpriseService.actions
          .updateShoppingList(service.cart)
          .then(remoteCart => {
            try {
              // Add new items added remotely
              service.cart = remoteCart
              service.cart.items = service.currentEnterpriseService.selectors.getItems()

              // Populate obfuscated rx item state
              service.setObfuscatedRxItems(service.cart)
              service.updateTimestamp()

              $rootScope.modifyingOrder = Boolean(remoteCart.order)
              $rootScope.modifyingInstacartOrder = Boolean(
                remoteCart.order && service.isInstacartOrder(remoteCart)
              )

              if (auth.user.status === 'trial') {
                localStorageService.deprecatedSet(
                  'trialCartInstance',
                  service.cart.cart_instance_id
                )
              }

              service
                .getCategorizedProductsAndCategories()
                .then(categorizedProductsAndCategories => {
                  shoppingListCache.setCache({
                    cart: service.cart,
                    categorizedProductsAndCategories,
                  })
                })

              return service.cart
            } finally {
              service.syncing = false
              service.itemsAtLastSync = service.countItems()
              service.itemsAtLastSyncForCartType = service.countItems(service.getCurrentCartType())
            }
          })
          .then(() => {
            validate && service.validateCart(validatePayload)
          })
          .catch(e => {
            service.syncing = false
            service.itemsAtLastSync = service.countItems()
            service.itemsAtLastSyncForCartType = service.countItems(service.getCurrentCartType())

            captureException(e)

            errorModalService.open(
              config.lang.defaultErrorMessage + config.lang.tryAgainPolite,
              lang.values.ok,
              null
            )
          })
      }

      service.setObfuscatedRxItems = cart => {
        const rxItems = getRxItemsList(cart.items)

        const requiresObfuscation = rxItems?.filter(
          item =>
            !Object.prototype.hasOwnProperty.call(service.obfuscatedRxItems, item.store_product.id)
        )
        if (requiresObfuscation.length > 0) {
          obfuscationHelper(requiresObfuscation).then(obfuscatedMap => {
            // merge existing state with new items
            service.obfuscatedRxItems = { ...service.obfuscatedRxItems, ...obfuscatedMap }
          })
        }
      }

      // Count the number of items in the cart, ignoring subscriptions
      service.countItems = function (type = null) {
        const getItemQuantity = item => {
          if (
            item.store_product &&
            (!item.store_product.product_type ||
              item.store_product.product_type !== 'subscription') &&
            enterpriseProductService.utils.isProductAvailable(item.store_product)
          ) {
            // When the item is a weighted product, just count the number of items to 1.
            return item.store_product.order_by_weight
              ? 1
              : item.quantity / item.store_product.ordering_parameters.increment
          }
          return 0
        }

        const items = type
          ? service.getCartTypeToItemsMap()[type]
          : service.getCartTypeToItemsMap()[service.getCurrentCartType()]

        if (!items) return 0

        return items.reduce((count, item) => count + getItemQuantity(item), 0)
      }

      service.countCustomItems = () => service.getCustomItems().length

      service.getCustomItems = () =>
        _.filter(service.cart && service.cart.items, { item_type: 'pseudo_product' })

      service.getNonCustomItems = () =>
        _.reject(service.cart && service.cart.items, { item_type: 'pseudo_product' })

      service.getCheckedItemsCount = () => {
        return service?.cart?.items?.filter(item => item.checked)?.length
      }

      // Get list of all cart products, [] otherwise
      // (and that are not pseudo/custom products - since pseudo products don't have a `store_product` key)
      service.getAllStoreProductsInCart = () => {
        return _.filter(
          config.useMultiContextCart
            ? // We should return all the items for MCOX because the products are relevant in every context now
              service.cart?.items
            : service.getCartTypeToItemsMap()[service.getCurrentCartType()],
          item => item.store_product
        )
      }

      // Get object with all categorized cart products grouped by associated category keys, UNCATEGORIZED otherwise
      service.getAllStoreProductsByCategory = () =>
        service
          .getAllStoreProductsInCart()
          .filter(item => item.store_product.product_type !== 'subscription')
          .reduce((out, item) => {
            const category = !enterpriseProductService.utils.isProductAvailable(item.store_product)
              ? lang.values.currentlyUnavailable
              : item.store_product?.categories?.[0]?.name || lang.values.uncategorizedCategory
            return { ...out, [category]: [...(out[category] || []), item] }
          }, {})

      // Returns categorized, uncategorized products and those categories in sorted order
      service.getCategorizedProductsAndCategories = () =>
        catalogCategoriesService.fetchCategories().then(() => {
          const { currentlyUnavailable, uncategorizedCategory, prescription } = lang.values
          const rootCategories = _.map(catalogCategoriesService.rootCategories(), 'name')
          rootCategories.unshift(uncategorizedCategory)
          rootCategories.push(currentlyUnavailable)
          rootCategories.push(prescription)

          const productsByCategory = service.getAllStoreProductsByCategory()
          const productCategories = _.keys(productsByCategory)

          // Sorted categorized product categories by order of root categories
          const orderedCategories = _.filter(
            rootCategories,
            rootCategory =>
              _.findIndex(productCategories, productCategory => productCategory === rootCategory) >
              -1
          )

          return { productsByCategory, orderedCategories }
        })

      // Get object with all cart products grouped by associated aisle, {} otherwise
      // (excluding pseudo/custom products - since pseudo products don't have a `store_product` key)
      service.getAllProductsByAisle = () => {
        return _.groupBy(service.getNonCustomItems(), item => {
          // Set aisle label to "Aisle Unknown" (or as defined in retailer config) if aisle is null
          return item.store_product && item.store_product.aisle
            ? item.store_product.aisle
            : config.unknownAisleText
        })
      }

      // Keep the trial user cart and use it to replace the logged in user's cart.
      // When the user logs in, the cart is automatically merged on the backend.
      // If we get here, we need to remove items that were not originally in
      // the trial users cart.
      service.sendUnataCommCartMessage = () => {
        unataCommService.send({
          eventType: unataCommService.EventType.CART,
          payload: service.unataCommCartPayload(service.cart),
        })
      }

      service.unataCommCartPayload = cart => {
        const cartTotalNumber = Number(parseFloat(cart.product_total).toFixed(2))
        const cartTotalCount = service.countItems()
        return { cart: { total: cartTotalNumber, count: cartTotalCount } }
      }

      // Listening for a 'unata-get-cart' message from client
      unataCommService.listen(unataCommService.EventType.GETCART, () =>
        service.sendUnataCommCartMessage()
      )

      service.isCartBelowMin = () => {
        const userHasEbtCardAndEbtItems =
          config.ebt.enabled && ebtCardService.userHasEbtCard && service.cartHasEbtItem()
        const cartMinimum = shoppingListService.commonUtils.getMinimumCartSpend()
        const cartTotals = service.getCartTotals()

        if (userHasEbtCardAndEbtItems) return true

        return (
          cartMinimum &&
          cartTotals &&
          cartTotals.pre_reward_program_discount_product_total < cartMinimum
        )
      }

      service.getPlaceOrderButtonText = () => {
        if ($rootScope.modifyingOrder) {
          return lang.values.updateOrder
        }
        return service.getCurrentCartType() === CART_TYPES.CATERING
          ? lang.values.checkoutSubmitInCatering
          : lang.values.checkoutSubmit
      }

      // get the cart summary text by shopping context/cart type.
      service.getCartSummaryTitleText = () => {
        if (service.getCurrentCartType() === CART_TYPES.CATERING) {
          return lang.values.pickupInStoreCatering
        }

        if (service.getCurrentCartType() === CART_TYPES.GROCERY) {
          if (auth.getCurrentShoppingMode() === SHOPPING_MODES.PICKUP) {
            return lang.values.pickupInstore
          }

          return lang.values.delivery
        }
      }

      service.getUpSellProductsInCart = upsellProducts => {
        const { items } = service.cart
        if (!items.length) return []

        const cartItemIds = items.map(item => item.id)
        return analytics
          .getProductListAnalytics(upsellProducts)
          .filter(product => cartItemIds.includes(product.id))
      }

      /**
       * Returns the *cart_id* for a given *cartType*
       *
       * @returns {Number}
       */
      service.getCartId = cartType => {
        return this.cart?.ids?.[cartType] || this.cart?.id
      }

      /**
       * Returns the *has_alcohol* for a given *cartType*
       *
       * @returns {Number}
       */
      service.getHasAlcohol = (cartType = '') => {
        const alcoholFromCartType = this.cart?.carts?.[cartType || this.getCurrentCartType()]
          ?.has_alcohol
        return alcoholFromCartType ?? this.cart?.has_alcohol
      }

      /**
       * Returns an array of all unique cart types the user has
       *
       * @returns {String[]}
       */
      service.getCartTypes = () => {
        return Array.from(
          new Set(
            this.cart.items.reduce((items, item) => {
              items.push(item.cart_type)
              return items
            }, [])
          )
        )
      }

      /**
       * Returns an object of cart items grouped by cartType
       *
       * @returns {Object}
       */
      service.getCartTypeToItemsMap = () => {
        /**
         * Empty all cart type items while keeping the object reference in memory.
         * Initially, this method was supposed to generate immutable data structures, but unfortunately
         * Angular doesn't play too well with it.
         *
         * This was a way i've found to avoid the `10 $digest() iterations reached. Aborting!` while
         * looping through items in the view depending on the user current cart type
         */
        Object.values(CART_TYPES).forEach(cartType => {
          service.itemsGroupedByCartType[cartType].splice(
            0,
            service.itemsGroupedByCartType[cartType].length
          )
        })

        if (service.cart) {
          service.cart.items.forEach(item => {
            if (item.cart_type) {
              service.itemsGroupedByCartType[item.cart_type].push(item)
            }
          })
        }

        return service.itemsGroupedByCartType
      }

      /**
       * Returns the *has_alcohol* for a given *cartType*
       *
       * @returns {Number}
       */
      service.getHasAlcohol = (cartType = '') => {
        const alcoholFromCartType = this.cart?.carts?.[cartType || this.getCurrentCartType()]
          ?.has_alcohol
        return alcoholFromCartType ?? this.cart?.has_alcohol
      }

      /**
       * Removes the rx items in the cart
       *
       * @returns {Promise}
       */
      service.removeRxItems = () => {
        return service.removeItems(service.cart.items.filter(cartItem => isRxProduct(cartItem)))
      }

      /**
       * Returns the user current cart type by checking the query param in the URL
       * or the eComLight status
       *
       * @returns {string}
       */
      service.getCurrentCartType = () => {
        if (!eComLightService.inEComLightMode) {
          return CART_TYPES.GROCERY
        }

        const { shopping_mode } = auth.user.context

        return shopping_mode === SHOPPING_MODES.CATERING ? CART_TYPES.CATERING : CART_TYPES.GROCERY
      }

      /**
       * Returns whether a user can order online
       *
       * It will be true if at least one non-eComLight mode is available.
       *
       * @returns {Boolean}
       */
      service.canOrderOnline = () => {
        const availableModes = [SHOPPING_MODES.DELIVERY, SHOPPING_MODES.PICKUP].filter(modeName => {
          return config.shoppingSelector[modeName]?.exists
        })

        // Require both delivery and pickup to be enabled, since one of them is being used for eComLight
        if (config.eComLight.enabled) {
          return availableModes.length > 1
        }

        return availableModes.length > 0
      }

      /**
       * Returns an object which maps categories to their corresponding cart type
       *
       * @returns {Object}
       */
      service.createCartTypeToCategoryMapping = () => {
        let firstProductInCategory = null
        const categorizedProducts = service.getAllStoreProductsByCategory()

        Object.keys(categorizedProducts).forEach(function (category) {
          firstProductInCategory = categorizedProducts[category][0]
          if (firstProductInCategory) {
            if (firstProductInCategory.cart_type === CART_TYPES.CATERING) {
              service.cartTypeToCategoryMapping.catering.push(category)
            } else {
              service.cartTypeToCategoryMapping.grocery.push(category)
            }
          }
        })
      }

      /**
       * Returns whether or not the given category corresponds to the current cart type
       *
       * @param { string } category
       *
       * @returns {Boolean}
       */
      service.isCategoryForCurrentCartType = category => {
        return service.cartTypeToCategoryMapping[service.getCurrentCartType()].includes(category)
      }

      /**
       * Returns an object that contains the cart totals depending on the
       * user current cart type
       *
       * @returns {Object}
       */
      service.getCartTotals = () => {
        return service.cart?.cart_totals?.[service.getCurrentCartType()] || service.cart
      }

      /**
       * Returns the product total for a specific cart type
       *
       * @returns {string}
       */
      service.getCartTotalForCurrentType = () => {
        const total = service.getCartTotals()
        return total?.product_total || 0
      }

      /**
       * Returns the cart final total
       *
       * @returns {string}
       */
      service.getCartFinalTotal = () => {
        return service.cartFinalTotal || service.getCartTotalForCurrentType()
      }

      /**
       * Returns the final tax total
       *
       * @returns {string}
       */
      service.getFinalTaxTotal = () => {
        return service.finalTaxTotal
      }

      /**
       * Setter for the cart final total
       *
       * @param {string}
       * @returns {undefined}
       */
      service.setCartFinalTotal = total => {
        service.cartFinalTotal = total
      }

      /**
       * Setter for the final tax total
       *
       * @param {string}
       * @returns {undefined}
       */
      service.setFinalTaxTotal = total => {
        service.finalTaxTotal = total
      }

      /**
       * Determines if tipping is enabled or not
       *
       * @returns {Boolean}
       */
      service.isTippingEnabled = () => {
        const { store } = auth.user
        return Boolean(store[`tipping_${auth.getCurrentShoppingMode()}`])
      }

      /**
       * Sets the cart tip to a percentage or absolute value
       *
       * @param { number } percentage
       * @param { number } absolute
       *
       * @returns {Promise}
       */
      service.setTip = ({ percentage = null, absolute = null } = {}) => {
        // eslint-disable-next-line no-restricted-globals
        // eslint-disable-next-line no-restricted-globals
        const isValidTipValue = val => val && !isNaN(parseFloat(val)) && isFinite(val)
        if (service.isTippingEnabled()) {
          service.cart.tip_percent = isValidTipValue(percentage)
            ? percentage
            : service.cart.tip_percent
          service.cart.tip_total = isValidTipValue(absolute) ? absolute : service.cart.tip_total
          return $q.resolve()
        }
        return $q.reject()
      }

      /**
       * Determines if the cart has an EBT item
       *
       * @returns {Boolean}
       */
      service.cartHasEbtItem = () => {
        return service.cart?.items.some(item => item.store_product?.usa_snap_eligible)
      }

      /**
       * Determines if the cart has an RX item
       *
       * @returns {Boolean}
       */
      service.cartHasRxItem = () => {
        return service.cart?.items.some(item => isRxProduct(item))
      }

      /**
       * Removes all the subscriptions in the cart
       *
       * @returns {Promise}
       */
      service.removeSubscriptions = () => {
        return service.removeItems(
          service.cart.items.filter(
            cartItem => cartItem.store_product.product_type === 'subscription'
          )
        )
      }

      service.getItemDescription = item => {
        if (isRxProduct(item)) {
          return service.obfuscatedRxItems[item.store_product.id] || item.store_product.name
        }
        return getItemDisplayName(item, config.hideSizeStringSideCart)
      }

      /**
       * Returns the authorization amount of the user current cart.
       * If the authorization amount is not present, it falls back to the FE calculation
       *
       * @returns {number}
       */
      service.getAuthorizationAmount = () => {
        const { authBuffer } = config
        const currentCartType = service.getCurrentCartType()
        const { cart } = service

        return (
          cart.carts?.[currentCartType]?.authorization_amount ||
          cart.authorization_amount ||
          (cart.cart_totals?.[currentCartType]?.total || cart.total) * authBuffer
        )
      }

      /**
       * Returns the user cart instance ID
       *
       * @returns {string} - UUID for the cart_instance_id
       */
      service.getCartInstanceId = () => {
        const currentCartType = service.getCurrentCartType()
        const { cart } = service

        return cart.carts?.[currentCartType]?.cart_instance_id || cart.cart_instance_id
      }

      service.removeSubscriptions = () => {
        const subscriptions = []
        service.cart.items.forEach(item => {
          if (item.store_product.product_type === 'subscription') {
            subscriptions.push(item)
          }
        })
        return service.removeItems(subscriptions)
      }

      /**
       * Gets the order delivery instructions from all possible sources
       *
       * @returns {{comment: string?, leaveUnattended: boolean?}}
       */
      service.getOrderDeliveryInstruction = () => {
        const { carts, comment, leave_unattended, order } = service.cart

        /*
         * order?.leave_unattended comes from: GET cart modify
         * cart.leave_unattended comes from: GET validate
         * carts?.grocery?.leave_unattended comes from: GET/PUT cart
         */
        const leaveUnattended = order?.leave_unattended
          ? order?.leave_unattended // Always go with the set value on modify
          : !service.getHasAlcohol() && (leave_unattended ?? carts?.grocery?.leave_unattended)

        return { comment, leaveUnattended }
      }

      /**
       * Gets the order delivery instructions for order modification case only
       *
       * @returns {{comment: string?, leaveUnattended: boolean?}}
       */
      service.getModifyOrderDeliveryInstruction = () => {
        const { comment, order } = service.cart
        const leaveUnattended = Boolean(order?.leave_unattended) // GET cart modify
        return { comment, leaveUnattended }
      }

      /**
       * Synchronizes new values for delivery instructions into the cart
       * @param {string}  comment
       * @param {boolean} leaveUnattended
       * @returns {Promise}
       */
      service.updateOrderDeliveryInstruction = (comment, leaveUnattended) => {
        Object.assign(service.cart, {
          comment,
          leave_unattended: Boolean(!service.getHasAlcohol() && leaveUnattended),
        })

        return service.sync()
      }

      $rootScope.$on('cartRefresh', (_, onAfterFetch) => {
        this.fetch().finally(() => {
          onAfterFetch && onAfterFetch()
        })
      })
    }
  )
  .run(
    (
      $location,
      $rootScope,
      $state,
      $timeout,
      Restangular,
      auth,
      cartService,
      config,
      errorModalService,
      instacartService,
      lang,
      localStorageService
    ) => {
      const addToCart = () => {
        const productId = $location.search().addToCart
        if (productId) {
          Restangular.one('store_products', productId)
            .get()
            .then(product => cartService.addItem(product))
        }
      }

      let orderModificationModal = null

      const checkModifyOrderStatus = () => {
        if (
          $rootScope.modifyingOrder &&
          !cartService.cart?.order?.can_modify &&
          !orderModificationModal
        ) {
          const {
            orderModifyUnavailable,
            ok,
            error,
            instacart: { viewOrderProgress },
          } = lang.values
          const cancelButton = cartService.isInstacartOrder(cartService.cart)
            ? viewOrderProgress
            : null

          orderModificationModal = errorModalService.open(
            orderModifyUnavailable,
            ok,
            cancelButton,
            error,
            true
          )
          orderModificationModal.result
            .catch(() =>
              instacartService.goToInstacartLink(cartService.cart.order.ext_data.pickup_status_url)
            )
            .finally(() => {
              cartService.cancelOrderModification()
              orderModificationModal = null
            })
        }
      }

      $rootScope.$on('$stateChangeSuccess', () => {
        const currentStateName = $state.current?.name
        const isCheckoutConfirmation = currentStateName === 'main.checkout.confirmation'
        const context = auth.getContext()

        // TODO: ENT-39451 - Do a proper fix, more details in the ticket
        // This is here as $state.current?.name is undefined on the onEnter and resolve functions of the route
        if (currentStateName === 'main.checkout') {
          $state.go('main.checkout.cart')
        }

        // If this gets set to true, when the modify banner shows it will auto-cancel the modify
        // We wait for the modify banner to ensure the cart/order state has been updated fully
        // (isTemporaryModify would usually appear in the context, but in some environments it
        // doesn't seem to stick, so we place it into local storage to be sure)
        const isTemporaryModify = context.isTemporaryModify
          ? context.isTemporaryModify
          : localStorageService.deprecatedGet('isTemporaryModify')

        $rootScope.isTemporaryModifyExpired =
          isTemporaryModify &&
          !isCheckoutConfirmation &&
          !currentStateName.includes(isTemporaryModify)

        addToCart()
        // This timeout exists to prevent the modal from being dismissed automatically due to a state change,
        // cancelling the order modification without notifying the user
        if (!isCheckoutConfirmation) {
          $timeout(() => checkModifyOrderStatus(), 1000)
        }
      })
    }
  )
