import angular from 'angular'
import { recaptcha } from '@instacart/enterprise-services'
import recaptchaModal from '../templates/recaptcha/recaptchaModal.html'

export default angular
  .module('recaptchaServiceModule', [])
  .service(
    'recaptchaService',
    function recaptchaService($q, config, errorModalService, utilService) {
      const service = this
      const recaptchaSdkLoaded = $q.defer()

      // Service API
      service.isEnabled = () => Boolean(config.googleRecaptcha?.enabled && !window.Cypress)
      service.isPaymentsEnabled = () => Boolean(config.googleRecaptcha?.paymentsEnabled)

      service.renderCheckbox = id => {
        return recaptchaSdkLoaded.promise
          .then(() => service.getClientSignature())
          .then(response => {
            window.grecaptcha.enterprise.render(id, { s: response.client_signature })
          })
          .catch(err => {
            errorModalService.open(err.message || config.lang.recaptchaLoadError, config.lang.ok)
          })
      }

      service.getClientSignature = () => {
        const successCallback = window.onRecaptchaCheckboxSuccess.toString()
        const callbackBody = successCallback
          // Remove the function signature
          .substring(successCallback.indexOf('{') + 1, successCallback.lastIndexOf('}'))
          // Remove all whitespace
          .replace(/\s+/g, '')
        return recaptcha.api.createClientSignature(window.location.href, callbackBody)
      }

      service.resetLastCheckbox = () => {
        return recaptchaSdkLoaded.promise
          .then(() => {
            window.grecaptcha.enterprise.reset()
          })
          .catch(err => {
            errorModalService.open(err.message || config.lang.recaptchaLoadError, config.lang.ok)
          })
      }

      // Service Run Block
      if (service.isEnabled()) {
        window.recaptchaOnLoad = () => {
          recaptchaSdkLoaded.resolve()
        }

        utilService
          .loadScript(config.googleRecaptcha?.url)
          .catch(err => recaptchaSdkLoaded.reject(err))
      }

      /**
       * Shows reCaptcha in a popup modal window. It should contain attribs as needed, specified in:
       *   app/angular/components/shared/recaptcha/recaptchaCheckbox.component.js
       *
       * @param {object} reCaptchaSetup  - Obejct of attribs
       *
       * @returns {Promise}  - Resolves if the recaptcha passes, rejects if the modal is closed
       */
      let modal = null

      service.openRecaptchaInModal = reCaptchaSetup => {
        modal = errorModalService.openCustom(recaptchaModal, { reCaptchaSetup })
        return modal.result
      }

      service.closeRecaptchaInModal = () => {
        if (modal) {
          modal.$dismiss()
          modal = null
        }
      }
    }
  )
