import angular from 'angular'
import template from './houseAccount.html'

export default angular
  .module('HouseAccountPaymentServiceModule', [])
  .config(paymentGatewayProvider => {
    paymentGatewayProvider.registerGateway({
      name: 'House Account',
      key: 'house_account',
      template,
      serviceName: 'houseAccountPayment',
      newCheckoutCssClass: 'provider-generic_styling',
    })
  })

  .service('houseAccountPayment', function houseAccountPayment($q, defaultPayments) {
    const service = this

    service.status = { loaded: true }

    service.checkout = defaultPayments.checkout
  })
