import formatSecondsToISO8601 from './formatSecondsToISO8601'
import { setJsonLdInHead, getCurrentJsonLd } from './jsonLd'

/**
 * Add script tag to head for product structured data
 *
 * @param {Object} product
 * @param {String} clientName
 */

export const appendProductStruturedData = (product, clientName = '') => {
  const scriptEl = getCurrentJsonLd()

  const data = {
    '@context': 'https://schema.org/',
    '@type': 'Product',
    name: product.name,
    image: Object.values(product.images?.tile || []),
    description: product.description || `${product.name} on ${clientName}`,
    sku: product.ext_id,
    brand: {
      '@type': 'Brand',
      name: product.brand_name,
    },
    aggregateRating: {
      '@type': 'AggregateRating',
      ratingValue: product.product_rating.average_rating,
      reviewCount: product.product_rating.user_count,
    },
  }

  setJsonLdInHead(scriptEl, data)
}

/**
 * Add script tag to head for recipe structured data
 *
 * @param {Object} recipe
 * @param {String} clientName
 */
export const appendRecipeStruturedData = (recipe, clientName = '') => {
  const scriptEl = getCurrentJsonLd()

  const caloriesQuantity = recipe.nutrients?.find(({ name }) => name === 'Calories')?.quantity

  const data = {
    '@context': 'https://schema.org/',
    '@type': 'Recipe',
    name: recipe.name,
    image: [recipe.media[0]?.image?.header],
    author: {
      '@type': 'Organization',
      name: clientName,
    },
    description: recipe.description || `${recipe.name} Recipe from ${clientName}`,
    prepTime: `${recipe.preparation_information.preparation_time}`,
    // keywords: recipe.categories?.reverse()[0].name, // TODO: Figure out how to do keywords
    recipeYield: recipe.preparation_information.serving_size,
    recipeCategory: [...recipe.categories]?.reverse()[0]?.name || '',
    // recipeCuisine: 'American', // TODO: Figure out how to do cuisine
    recipeIngredient: recipe.ingredients,
    recipeInstructions: recipe.directions,
    aggregateRating: {
      '@type': 'AggregateRating',
      ratingValue: recipe.recipe_rating.average_rating,
      ratingCount: recipe.recipe_rating.user_count,
    },
  }

  if (caloriesQuantity) {
    data.nutrition = {
      '@type': 'NutritionInformation',
      calories: `${caloriesQuantity} calories`,
    }
  }

  if (recipe.preparation_information?.preparation_time)
    data.prepTime = formatSecondsToISO8601(recipe.preparation_information?.preparation_time)

  if (recipe.preparation_information?.cook_time)
    data.cookTime = formatSecondsToISO8601(recipe.preparation_information?.cook_time)

  if (recipe.preparation_information?.total_time)
    data.totalTime = formatSecondsToISO8601(recipe.preparation_information?.total_time)

  setJsonLdInHead(scriptEl, data)
}

export const appendItemListStructuredData = recipes => {
  const scriptEl = getCurrentJsonLd()
  const origin = window?.location?.origin || ''
  const data = {
    '@context': 'https://schema.org',
    '@type': 'ItemList',
    itemListElement: [],
  }

  Object.values(recipes)
    .filter(recipe => recipe && recipe.id)
    .forEach((recipe, idx) => {
      if (recipe.href) {
        data.itemListElement.push({
          '@type': 'ListItem',
          position: idx + 1,
          url: `${origin}${recipe.href}`,
        })
      }
    })

  setJsonLdInHead(scriptEl, data)
}
