import angular from 'angular'
import { windowManager } from '@instacart/enterprise-services-cordova'
import { UnataComm, EventType } from '@instacart/enterprise-services-unata-comm'

export default angular
  .module('mobileNativeHrefServicesModule', [])
  .service(
    'mobileNativeHrefService',
    function mobileNativeHrefService($injector, $location, $rootScope, auth) {
      const service = this

      const unataComm = new UnataComm()

      /**
       * Public Methods
       */

      service.openAuthUrl = windowManager.openAuthUrl

      service.waitForBrowserClose = windowManager.waitForBrowserClose

      // This was added in because FirstDataV2 JS SDK does not work correctly inside cordova.
      // This loads the checkout page inside the InAppBrowser and injects the script which reloads
      // the page when it's no longer inside the checkout flow.
      // https://instacart.quip.com/wGX6AC58bxuT/FirstData-V2-Javascript-Issue
      service.openCheckoutInInappBrowser = urlPath => {
        windowManager.openPlatformPath(urlPath, {
          closeOnReload: true,
          code: `(
          function() {
            setTimeout(
              function() {
                var states = [
                  'main.checkout.cart',
                  'main.checkout.cartV2',
                  'main.checkout.manualUpsell',
                  'main.checkout.reservation',
                  'main.checkout.reservation.address',
                  'main.checkout.reservation.confirmation',
                  'main.checkout.payment',
                  'main.checkout.confirmation',
                  'main.checkout.customUpsell',
                  'main.checkout.checkoutPage'
                ];
                var rootScope = angular.element(document.body)
                  .injector()
                  .get('$rootScope');
                window.inAppBrowser = true;
                rootScope.$on('$locationChangeSuccess', function(a) {
                  var state = angular.element(document.body).injector().get('$state');
                  if (states.indexOf(state.current.name) === -1) {
                    window.location.reload();
                  }
                });
              }, 250
            )
            return true;
          })()
        `,
          onClose: () => {
            // Fetch the cart again after the checkout flow closes
            // eslint-disable-next-line no-unused-expressions
            $injector.get('cartService')?.fetch()
          },
        })
      }

      /**
       * Private Methods
       */

      const onNavigateToUrl = event => {
        $location.url(event.data.payload.url)

        // $apply only if not already in $apply.
        if (!$rootScope.$$phase) {
          $rootScope.$apply()
        }
      }

      const onReloadUser = _event => {
        auth.reloadIfUserChanged()
      }

      /**
       * Event Listeners
       */

      // Important! Sets up global listeners for clicks, window opens, and comms
      windowManager.listen()

      unataComm.listen(EventType.NAVIGATETOURL, onNavigateToUrl)

      unataComm.listen(EventType.RELOADUSER, onReloadUser)

      unataComm.listen(EventType.OPENSIDECART, () => {
        $rootScope.$broadcast('sideCart', 'open')
      })

      $rootScope.$on('$locationChangeStart', (_event, newUrl, oldUrl) => {
        const payload = {
          old_url: oldUrl,
          new_url: newUrl,
          platform: 'mobile-app',
        }

        unataComm.send({
          eventType: EventType.LOCATIONCHANGE,
          payload,
        })
      })
    }
  )
