import _ from 'lodash'
import angular from 'angular'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'

export default angular
  .module('timeslotServicesModule', [])
  .service(
    'timeslotService',
    function timeslotService(
      $q,
      $state,
      Restangular,
      auth,
      config,
      errorModalService,
      instacartCustomerLinkModalService,
      lang,
      shoppingContextService,
      shoppingSelectorService
    ) {
      const service = this

      service.getTimeslots = (intent, cartId, notifyOnError = true) => {
        const above_threshold = $state.current.name !== 'main.checkout.checkoutPage'

        return Restangular.all('timeslots')
          .getList({
            fulfillment_type: intent,
            above_threshold,
            cart_id: cartId,
            user_timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
          })
          .catch(response => {
            if (
              config.instacart.enableAccountLinking &&
              response.data?.error === HTTP_ERRORS.EXTERNAL_AUTH_ERROR
            ) {
              instacartCustomerLinkModalService
                .openModal()
                .then(
                  () => service.areTimeslotsSelectable() && shoppingSelectorService.open('timeslot')
                )
                .catch(() => {
                  if ($state.current.name === 'main.checkout.checkoutPage') {
                    $state.go('main.checkout.cart')
                  }
                })
            } else if (notifyOnError) {
              errorModalService
                .open(lang.values.cantLoadTimeslots, lang.values.reload, lang.values.backToHome)
                .then($state.reload, () => $state.go('main.home'))
            } else {
              console.error(lang.values.cantLoadTimeslots, response)
            }
          })
      }

      service.getPreviewTimeslot = (cartId, notifyOnError) => {
        return service
          .getTimeslots(auth.user.context.intent, cartId, notifyOnError)
          .then(response => {
            if (!response?.length) {
              return null
            }
            const immediate_timeslots = _.sortBy(
              _.filter(
                response,
                t => t.timeslot.polytype === 'immediate' && !t.timeslot.unavailable
              ),
              ['fulfillment_date', 'timeslot.within_hours']
            )
            if (immediate_timeslots.length) {
              return immediate_timeslots[0].plain()
            }

            const scheduled_timeslots = _.orderBy(response, [
              'fulfillment_date',
              'timeslot.from_time',
            ])

            return scheduled_timeslots[0].plain()
          })
      }

      service.areTimeslotsSelectableAtCheckoutOnly = () => {
        return config.shoppingContextProcess[shoppingContextService.getIntentFromContext()]
          ?.timeslotSelectionAtCheckoutOnly
      }

      service.areTimeslotsSelectable = () => {
        if (!service.areTimeslotsSelectableAtCheckoutOnly()) return true

        return [
          'main.checkout.payment',
          'main.checkout.checkoutPage',
          'main.checkout.reservation.confirmation',
        ].includes($state.current.name)
      }
    }
  )
