import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('addressSearchServicesModule', [])
  .service('addressSearchService', function addressSearchService($q, config, utilService) {
    const service = this

    const path = `https://maps.googleapis.com/maps/api/js?key=${config.googlePlacesAPIKey}&libraries=places`
    const sdkLoadedDefer = $q.defer()

    utilService
      .loadScript(path)
      .then(() => sdkLoadedDefer.resolve())
      .catch(err => sdkLoadedDefer.reject(err))

    // This service is hardcoded for US only.
    const POSTAL_CODE_LENGTH = 5
    const getTruncatedPostalCode = code => {
      return code?.length > POSTAL_CODE_LENGTH ? code.slice(0, POSTAL_CODE_LENGTH) : code
    }

    const findFirstObject = (components, name, attr = 'long_name') => {
      const obj = _.find(components, o => o.types.indexOf(name) > -1)
      return obj && obj[attr]
    }

    const parseFieldsFromAutocomplete = place => {
      const components = place.address_components
      const address = findFirstObject(components, 'street_number')
      return {
        address1: `${address ? `${address} ` : ''}${findFirstObject(components, 'route')}`,
        city:
          findFirstObject(components, 'locality') ||
          findFirstObject(components, 'administrative_area_level_3'),
        province: findFirstObject(components, 'administrative_area_level_1'),
        province_short: findFirstObject(components, 'administrative_area_level_1', 'short_name'),
        postal_code: getTruncatedPostalCode(findFirstObject(components, 'postal_code')),
      }
    }

    service.sdkLoaded = sdkLoadedDefer.promise

    service.addAutoCompleteWidget = (input, callback) => {
      const options = {
        types: ['address'],
        componentRestrictions: {
          country: 'us',
        },
      }

      const autocomplete = new google.maps.places.Autocomplete(input, options)
      // Limit fields for cost optimization
      autocomplete.setFields(['address_components', 'geometry', 'icon', 'name'])

      const autocompleteListener = google.maps.event.addListener(
        autocomplete,
        'place_changed',
        () => {
          const place = autocomplete.getPlace()

          if (callback) {
            // No address found
            if (!place.geometry || !place.geometry.location) {
              callback({
                address1: null,
                city: null,
                province: null,
                province_short: null,
                postal_code: null,
              })
            } else {
              callback(parseFieldsFromAutocomplete(place))
            }
          }
        }
      )

      return [autocomplete, autocompleteListener]
    }

    service.removeAutoCompleteWidget = (autoComplete, autoCompleteListener, pacContainer) => {
      google.maps.event.removeListener(autoCompleteListener)
      google.maps.event.clearInstanceListeners(autoComplete)
      pacContainer.remove()
    }
  })
