import angular from 'angular'
import { v4 as uuidv4 } from 'uuid'
import { REFLECTION } from '@app/angular/services/reflect'
// TODO: Fix me
// eslint-disable-next-line import/no-cycle
import { EVENTS } from '@app/angular/services/modules/analytics/analyticsConstants'

export const REFRESH_MS = 500
export const STORE_TIMEOUT_MS = 1000 * 60 * 5

/**
 * A useful "SUCCESS" analytic that can be included with your analytics constants - automatically optional!
 */
const SUCCESS = 'success'
const AUTO_SUCCESS = sources => (SUCCESS in sources ? { success: sources[SUCCESS] } : {})

/**
 * "@Requirements"
 * A decorator to check the requirements of analytic methods are present
 * This wraps the original function in a parent function that detects if the requirements are there before proceeding
 *
 * @param {[string]} requirements[<string>]    - A list of source requirements
 *
 * @example
   class CART_ANALYTICS {
     @Requirements(SOURCES.CART)
     INFO_ANALYTICS = (sources) => ({ cart: etc(sources[SOURCES.CART]) });
     ...
 *
 */
export const Requirements = (...requirements) => {
  return targetFn => {
    const pluralReqs = targetFn.initializer.pluralRequirements || {}

    REFLECTION.DECORATORS.wrapOriginalFn(targetFn, function (source = {}) {
      requirements.forEach(requirement => {
        if (!(requirement in source) && !(pluralReqs[requirement] in source)) {
          throw new Error(`\nMissing source for checkout analytics! Expected: "${requirement}"`)
        }
      })

      return this.callOriginalFn()
    })

    targetFn.initializer.pluralRequirements = null
  }
}

/**
 * "@Plural"
 * A decorator to allow analytical functions to process plural versions of the source input
 * This wraps the original function in a parent function that detects if a plural source key word is present
 * If it detects a plural source key word, it then triggers the original function multiple times, flattens the result
 * and outputs it in a plural analytics keyword (captures the key used and adds the pluralPostix etc)
 *
 * @param {string} sourceItemKey              - The key used for a single source item as normal
 * @param {function} [pluralPostixFn = s]     - The postfix to add to the keys for plural keys, defaults to 's'
 * @param {function} [destPluralPostixFn]     - [optional] If you want a separate plural key for payloads
 *
 * NOTE: Most plurals in EN are simply "+s", in which case do not specify the last two parameters (see examples below)
 *
 * @example 1 - **Basic example**
   class OFFER_ANALYTICS {
     // OFFER_DETAILS will now support SOURCES.OFFER + "s" array input, and output array "offers"
     // "offers" --> ["offer"] ~~~PROCESSING~~~> ["offer"] --> "offers"
     @Plural(SOURCES.OFFER)
     @Requirements(SOURCES.OFFER)
      OFFER_DETAILS = (sources) => ({ offer         : getOfferDetails(sources[SOURCES.OFFER]) })
   }
 *
 * @example 2 - **Complex example (using custom keys)**
   class OFFER_ANALYTICS {
     // OFFER_DETAILS will now support SOURCES.OFFER + "ies" array input, and output array "offeres"
     // "offeries" --> ["offer"] ~~~PROCESSING~~~> ["offer"] --> "offeres"
     // If you prefer, you can use the second parameter for both in and out keys, as the last parameter is optional
     @Plural(SOURCES.OFFER, (key) => `${key}ies`, (key) => `${key}es`)
     @Requirements(SOURCES.OFFER)
      OFFER_DETAILS = (sources) => ({ offer         : getOfferDetails(sources[SOURCES.OFFER]) })
   }
 *
 */
export const Plural = (
  sourceItemKey,
  pluralPostixFn = key => `${key}s`,
  destPluralPostixFn = null
) => {
  const sourcePluralKey = pluralPostixFn(sourceItemKey)

  return targetFn => {
    if (targetFn.initializer.pluralRequirements === null) {
      throw new Error('@Plural must come after @Requirements')
    }

    REFLECTION.DECORATORS.wrapOriginalFn(targetFn, function (sources = {}) {
      const pluralArray = sources[sourcePluralKey] // Get plural from the sources

      if (Array.isArray(pluralArray)) {
        // Map the plural array source to flattened prune analytical payloads
        const destKeys = []
        const results = pluralArray
          .map(input => this.callOriginalFn(undefined, [{ ...sources, [sourceItemKey]: input }]))
          .map(output => getValue(output, destKeys))
          .filter(item => !!item)

        const destPluralKey = destPluralPostixFn
          ? destPluralPostixFn(destKeys[0])
          : pluralPostixFn(destKeys[0])
        return { [destPluralKey]: results }
      }

      return this.callOriginalFn()
    })

    // To be used by @Requirements as an alternative to sourceItemKey
    targetFn.initializer.pluralRequirements = { [sourceItemKey]: sourcePluralKey }
  }
}

/*
 * Gets a value from an object that has only one key and adds it to the output array
 * Since only one key is allowed it throws an error if there is more than one key, either immediately or overall
 */
function getValue(obj, outKeys) {
  const keys = Object.keys(obj)
  const firstKey = keys[0]

  if (!outKeys.includes(firstKey)) {
    outKeys.push(firstKey)
  }

  if (keys > 1 || outKeys.length > 1) {
    throw new Error('@Plural cannot be used with output containing more than 1 key ever')
  }

  return obj[firstKey]
}

/**
 * An Angular helper service for using in your main analytics service
 */
export default angular
  .module('AnalyticsHelperModule', [])
  .service('analyticsHelper', function ($timeout, analytics, localStorageService, utilService) {
    const service = this

    Object.assign(service, {
      $timeout,
      analytics,
      localStorageService,
      utilService,
    })

    /**
     * Helper method to get analytic payloads
     *
     * @param {object} source - Object of data to be used to generate analytic payloads
     * @param {array}  requiredInfo - Array of constants defined in the ANALYTICS constant
     *
     * @returns {object|null}
     *
     * @example
          const analyticsPayload = analyticsHelper.createPayload({ ...this.DEFAULTS }, [ ANALYTICS.FLOW.INSTANCE ]);
     *
     */
    service.createPayload = function (source = {}, requiredInfo = []) {
      try {
        requiredInfo.push(AUTO_SUCCESS)

        // Get the payload based on the passed requiredInfo, automatically include success if present
        return requiredInfo.reduce((out, factory) => Object.assign(out, factory(source)), {})
      } catch (e) {
        console.error(e, source, requiredInfo)
        return null
      }
    }

    /**
     * Private: Helper method to post analytic payloads
     *
     * @param {string} eventName         - The event name to be used
     * @param {object} analyticsPayload  - The generated analyticsPayload
     * @param {object} analyticExtras    - Manual extras to be added to the payload
     *
     * @returns {undefined}
     */
    service.logAnalytics = function (eventName, analyticsPayload, analyticExtras = {}) {
      try {
        if (analyticsPayload) {
          service.analytics.logSegmentEvent(eventName, {
            ...analyticsPayload,
            ...analyticExtras,
          })
        } else {
          console.warn(`Empty analytics payload passed for ${eventName}`)
        }
      } catch (e) {
        // Errors are caught because we don't want errors caused by logging to disrupt normal developer flow
        console.error(e)
      }
    }

    /**
     * Provides a mechanism to track a true|false source value and when, and only when, that value changes, to respond by
     * calling a setter to set a variable elsewhere.
     *
     * This method should be called in a constructor and returns to you a function that you call with true or false
     * as you enter and exit parts of the UI (e.g. "Am I in the cart? true or false").
     *
     * The setter is passed a UUID, for you to use as you wish, setting into a variable.
     * A corresponding getter is used to query that variable (really only cares if it is set or null)
     * The same UUID is stored to local storage, so when you refresh the setter is automatically passed the last UUID
     * if there was one. After a delay, it will check to see if you have confirmed that a value should be set (by
     * a call of the returned function from this factory). If a confirmation is not received, it assumes the user did
     * not merely refresh the page, but left and navigated away: In this case the setter will be called again with null.
     *
     * Note: Values stored in the store are given an expiry time after which the stored UUID will be forgotten.
     *
     *
     * @param {string}                               localStoreId - The string ID to use for the local store
     * @param {function<*|null>}                     getValueFn   - The function to get the variable contents (either something or null)
     * @param {function(string<uuid>|null, boolean)} setValueFn   - The function that is called when there is a change in the isEnter value
     *                                                              1ST ARG: A UUID if entering, or null if exiting; 2ND ARG: Rehydrating initial set (true|false)
     * @param {int}                                  [refreshDelay] - Optional: Amount of time to wait before confirming the rehydrated UUID upon refresh/later visit
     * @param {int}                                  [storeTimeout] - Optional: The maximum time to store a value in the localstore before it expires
     *
     * @returns {function(boolean)}         - A function you can call with has entered (true) or exited (false) area of interest for tracking
     *
     * @example
           constructor(
             analyticsHelper,
             ) {
              ....
              ....

              const getCheckoutUUID = () => this.checkoutUUID;
              const setCheckoutUUIDAndFireEvents = (newChange, isInitial) => {
                this.checkoutUUID = newChange;
                if (!isInitial) {
                  $timeout(() => newChange ? this.enterCheckoutFlow(this.DEFAULTS) : this.exitCheckoutFlow(this.DEFAULTS));
                }
              };

              // Creates a function we can use to track entering/exiting the checkout area
              this.isInNewCheckoutFlowFn = analyticsHelper.persistentStateFactory(
                CHECKOUT_UUID_STORE,
                getCheckoutUUID,
                setCheckoutUUIDAndFireEvents
              );
            }
     *
     */
    service.persistentStateFactory = function (
      localStoreId,
      getValueFn,
      setValueFn,
      refreshDelay = REFRESH_MS,
      storeTimeout = STORE_TIMEOUT_MS
    ) {
      const { $timeout, utilService } = service
      let isConfirmed = false

      // Set the initial value from the store
      const lastValue = utilService.getExpiringValueFromStore(localStoreId) || null
      setValueFn(lastValue, true)

      // If the user refreshes the page and a getValueFn() is set, but the router never confirmed there was a value.
      // Then we need to set value to null because the user didn't refresh, they navigated away.
      $timeout(() => {
        if (getValueFn() !== null && !isConfirmed) {
          setValueFn(null, false)
        }
      }, refreshDelay)

      // Returns a function that only calls setValueFn if there is a change in `isEnter` value
      // It always updates the loca storage so that the time stamp is updated
      // This value will persist between
      return isEnter => {
        if (getValueFn() === null && isEnter) {
          setValueFn(uuidv4(), false)
        }

        if (getValueFn() && !isEnter) {
          setValueFn(null, false)
        }

        utilService.setExpiringValueToStore(localStoreId, getValueFn(), storeTimeout)
        isConfirmed = isEnter
      }
    }

    /**
     * This function fires an Availability.view event when the timeSlots are fetched
     *
     *  @param {Array}                               timeSlots - Array containing loaded timeslots
     */
    service.availabilityViewEvent = function (timeSlots) {
      const timeSlotPayload = { delivery_related: [] }
      const { warehouse_location_id, warehouse_id } = timeSlots
      timeSlots.forEach(slot => {
        timeSlotPayload.delivery_related.push({
          delivery_fee: slot.timeslot.shop_fee,
          available_ind: !slot.timeslot.unavailable,
          delivery_option_name: slot.timeslot.within_hours,
          delivery_option_id: slot.timeslot.id,
        })
      })
      const payload = { ...timeSlotPayload, warehouse_location_id, warehouse_id }
      this.logAnalytics(EVENTS.CHECKOUT.VIEW_AVAILABILITY, payload)
    }
  })
