import angular from 'angular'
import { DEFAULT_TIMESLOT_DATE_FORMAT } from '@app/angular/constants/timeslots'
import { imageTransform, utils } from '@instacart/enterprise-services'
import { utils as timeslotUtils } from '@instacart/enterprise-timeslots'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'

const ACKNOWLEDGED_HEADER_BANNERS = 'acknowledgedHeaderBanners'

export default angular
  .module('NavControllerModule', [])
  .controller(
    'NavController',
    function (
      $rootScope,
      $scope,
      $state,
      $timeout,
      $window,
      analytics,
      auth,
      cartService,
      config,
      eComLightService,
      fulfillmentNotAvailableModalService,
      headerFooterSyncService,
      lang,
      localStorageService,
      loyaltyInfoService,
      loyaltyOnboardingService,
      mobileService,
      placementsService,
      registrationService,
      reservationService,
      shoppingContextService,
      shoppingSelectorService,
      toggleEComLightService,
      userService,
      utilService
    ) {
      $scope.utilService = utilService
      $scope.auth = auth
      $scope.cartService = cartService
      $scope.shoppingContextService = shoppingContextService
      $scope.config = config
      $scope.registrationService = registrationService
      $scope.shoppingSelectorService = shoppingSelectorService
      $scope.headerFooterSyncService = headerFooterSyncService
      $scope.state = $state
      $scope.reservationService = reservationService
      $scope.imageTransform = imageTransform
      $scope.hideSlidingLogo = false
      $scope.showHidingLogo = false
      $scope.searchTerm =
        $state.params && $state.params.search_term ? $state.params.search_term : ''

      $scope.inEComLightMode = eComLightService.inEComLightMode
      $scope.toggleEComLightMode = toggleEComLightService.toggleEComLightMode

      $scope.enableToggleEcomLightMode =
        config.shoppingSelector[SHOPPING_MODES.CATERING].exists &&
        (config.shoppingSelector[SHOPPING_MODES.INSTORE_LIST].exists ||
          config.shoppingSelector[SHOPPING_MODES.PICKUP].exists)

      $scope.eComLightModeToggleText =
        $scope.inEComLightMode && config.shoppingSelector[SHOPPING_MODES.INSTORE_LIST].exists
          ? config.eComLight.headerLinkFrom
          : $scope.inEComLightMode && config.shoppingSelector[SHOPPING_MODES.PICKUP].exists
          ? config.eComLight.headerLinkPickup
          : config.eComLight.headerLinkTo

      $scope.ecomLightModeToggleIcon = eComLightService.eComLightLinkIcon
      $scope.afterHeaderLoaded = headerFooterSyncService.afterHeaderLoaded
      $scope.stickyNavLogo = utilService.loadAssetImage(config.stickyNavLogoPath)
      $scope.privateMode = utils.isPrivateMode()
      $scope.privateModeMessage = lang.template(lang.values.privateModeMessage, {
        customerName: auth.user.first_name,
      })

      const reactHeaderEnabled = mobileService.isMobile
        ? config.react?.enableMobileHeader
        : config.react?.enableHeader
      $scope.useAngularHeader = !(reactHeaderEnabled || $rootScope.isReactDemo())

      $scope.openLoyalty = function () {
        if (process.env.UNATA_WIDGETS) {
          $window.open(`${config.env.webUrl}/account/loyalty`, '_blank')
          return
        }
        if (userService.loyaltyCards.length >= 1) {
          $state.go('main.account.loyalty')
        } else if (config.enableLoyaltyOnboarding) {
          loyaltyOnboardingService.initOnboarding('intro')
        } else {
          loyaltyInfoService.open()
        }
      }

      eventEmitter.on(EVENT_TYPES.OPEN_LOYALTY_CONTEXT, $scope.openLoyalty)

      $scope.$on('$destroy', () => {
        eventEmitter.off(EVENT_TYPES.OPEN_LOYALTY_CONTEXT, $scope.openLoyalty)
      })

      $scope.showCheckoutButton = () => {
        return !['main.checkout.cart', 'main.checkout.cartV2', 'main.inStoreList.review'].includes(
          $state.current.name
        )
      }

      //
      // Header Banner
      //
      let headerBanners = []
      placementsService.getHeaderBanners().then(headerBannersResponse => {
        // Add a UI URL to collection targets
        headerBannersResponse.forEach(banner => {
          if (banner.target?.type === 'collection') {
            banner.target.url = $state.href('main.shop.collection', {
              collection_id: banner.target.collection.id,
            })
          }
        })

        $scope.logPromotedPlacement = placementsService.logPromotedPlacement

        function hasShowableBanners() {
          if (config.react.enableHeaderBanner) {
            return false
          }

          return headerBanners.filter(banner => banner.media.text || banner.media.image).length > 0
        }

        function acknowledgeHeaderBanner(banner, shouldAcknowledge = true) {
          if (shouldAcknowledge) {
            const acknowledgedHeaderBanners =
              localStorageService.deprecatedGet(ACKNOWLEDGED_HEADER_BANNERS) || []
            acknowledgedHeaderBanners.push(banner.id)
            localStorageService.deprecatedSet(
              ACKNOWLEDGED_HEADER_BANNERS,
              acknowledgedHeaderBanners
            )
            updateBanners()
          }
        }

        function updateBanners() {
          const acknowledged = localStorageService.deprecatedGet(ACKNOWLEDGED_HEADER_BANNERS) || []
          headerBanners = headerBannersResponse.filter(banner => !acknowledged.includes(banner.id))

          // FIXME: This is a brutal hack for header banner positioning, we really need to add a root container
          // div to the index page so that all of these ui-view containers & heights can follow the natural DOM
          // flow rather than the siblings under a <body> tag relationship they have right now.
          const bannerParams = { headerBanners, hasShowableBanners, acknowledgeHeaderBanner }
          Object.assign($scope, bannerParams)
          Object.assign($rootScope, bannerParams)

          $timeout(() => $scope.$apply()) // $timeout ensures that this isn't called while a digest is in progress

          analytics.logPlacementImpression(headerBanners.slice(0, 1), 'homepage', 'header_banner')
        }

        updateBanners()

        $scope.bannerTarget = (target = {}) => {
          return target.openIn === 'new_tab' ? '_blank' : undefined
        }

        // Call template for header sync and when using angular header instead of react
        if (config.dotComModule.enableHeaderFooterSync && $scope.useAngularHeader) {
          $scope.dotComHeaderIsLoading = true
          $scope.headerFooterSyncService
            .fetchHeader()
            .then(html => {
              $scope.dotComHeader = html
              $scope.headerSyncError = false
            })
            .catch(() => {
              $scope.headerSyncError = true
            })
            .finally(() => {
              $scope.dotComHeaderIsLoading = false
            })
        }

        $scope.timeslotLabelFn = () =>
          timeslotUtils.getMainLabel($scope.reservationService.displayReservation, {
            verboseLevel: 1,
            format: reservationService.reservation
              ? config.apiDateFormat
              : DEFAULT_TIMESLOT_DATE_FORMAT,
          })
      })
    }
  )
  .run($templateCache => {
    $templateCache.put('nav.desktop.html', require('./views/nav.desktop.html'))
    $templateCache.put('nav.mobile.html', require('./views/nav.mobile.html'))
    $templateCache.put('sideNav.html', require('./views/sideNav.html'))
  })
