// Compile directive that acts as an advanced ng-bind-html that can compile directives/components

import angular from 'angular'

export default angular.module('CompileComponentModule', []).directive('compile', $compile => {
  return function (scope, element, attrs) {
    scope.$watch(
      scope => {
        return scope.$eval(attrs.compile)
      },
      value => {
        element.html(value)
        $compile(element.contents())(scope)
      }
    )
  }
})
