import { datadogRum } from '@datadog/browser-rum'
import { getCurrentShoppingMode } from '@app/utils/getCurrentShoppingMode'
import { auth as authService, config as configService } from '@instacart/enterprise-services'
import { DATADOG_CART_ACTIONS, METRIC_OWNER } from './constants'
import { oneCartEnabled } from '../oneCart/utils'

function actionMetaData() {
  const config = configService.selectors.getConfig()
  const userContext = authService.selectors.getUserContext()
  const user = authService.selectors.getUser()
  const mode = getCurrentShoppingMode(userContext, config)
  const oneCart = oneCartEnabled()

  return {
    ic_user_id: user?.ic_user_id,
    is_one_cart: oneCart,
    owner: METRIC_OWNER,
    shopping_mode: mode,
    user_id: user?.id,
  }
}

function sideCartCheckoutEvent() {
  try {
    datadogRum.addAction(DATADOG_CART_ACTIONS.SIDE_CART.CHECKOUT, { ...actionMetaData() })
  } catch (error) {
    // do nothing
  }
}

export default {
  sideCart: {
    checkout: sideCartCheckoutEvent,
  },
}
