/* eslint-disable max-lines */

import _ from 'lodash'
import angular from 'angular'
import { analytics as enterpriseAnalytics } from '@instacart/enterprise-services'
import getHtml5Url from '@app/utils/getHtml5Url'
import isMobileApp from '@app/utils/isMobileApp'
import { SEGMENT_SPONSORED_SOURCE } from '@app/angular/services/modules/analytics/analyticsConstants'
import { v4 as uuidv4 } from 'uuid'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import { getBannerSegmentPayload } from '@app/utils/banners'
import waitOneTrust from '@app/utils/waitOneTrust'

const FP_IMPRESSION_KEY = 'fp_impression'
const FP_IMPRESSION_EXPIRATION = 60 * 1000 * 5 // 5 minutes

const PLACEMENT_IMPRESSION_KEY = 'active_placement_impression'
const PLACEMENT_IMPRESSION_EXPIRATION = 1000 * 60 * 10 // 10 minutes

export default angular
  .module('AnalyticsServicesModule', [])
  .service(
    'analytics',
    function analytics(
      $document,
      $location,
      $q,
      $rootScope,
      $state,
      $window,
      anonymousIdService,
      auth,
      config,
      mobileService,
      searchService,
      unataCommService,
      universalLinksService,
      userService,
      utilService
    ) {
      const {
        integrations,
        hotjarId = null,
        googleAnalyticsID = null,
        googleTagManagerID: googleTagManagerIDWeb = null,
        googleTagManagerIDNativeMobile = null,
        segmentID = null,
        appsFlyerSDK,
        sendAnalyticsOnStage,
        env,
        oneTrustSDK,
      } = config

      const service = this

      service.featuredProductImpressions = {}
      service.placementImpressions = {}

      const shouldFireAnalytics = env.mode === 'prod' || sendAnalyticsOnStage === true
      const googleTagManagerID = mobileService.isMobileApp()
        ? googleTagManagerIDNativeMobile
        : googleTagManagerIDWeb

      const analyticsServicesMap = {
        googleAnalytics: googleAnalyticsID,
        googleTagManager: googleTagManagerID,
        hotjar: hotjarId,
        segment: segmentID,
      }

      const analyticsServicesArray = Object.entries(analyticsServicesMap)
        .map(([name, id]) => ({ name, id }))
        .filter(service => service.id !== null)

      let previousStateName
      $rootScope.$on('$stateChangeSuccess', (ev, to, toParams, from) => {
        previousStateName = from.name
      })

      if (
        isMobileApp() &&
        appsFlyerSDK?.enabled &&
        appsFlyerSDK?.devKey &&
        appsFlyerSDK?.appleAppId &&
        window?.plugins?.appsFlyer
      ) {
        const options = {
          devKey: appsFlyerSDK.devKey,
          appId: appsFlyerSDK.appleAppId,
          onInstallConversionDataListener: true,
          timeToWaitForAdvertiserID: 10, // https://github.com/AppsFlyerSDK/appsflyer-cordova-plugin#--setup
        }

        const appsFlyerLinkHandler = result => {
          try {
            const deeplinkData = JSON.parse(result)
            const url = deeplinkData?.data?.deep_link_value
            if (deeplinkData?.type === 'onAppOpenAttribution') {
              if (url) {
                universalLinksService.didLaunchAppFromLink({ url })
              }
            } else if (deeplinkData?.type === 'onInstallConversionDataLoaded') {
              if (deeplinkData?.data?.is_first_launch === true) {
                if (url) {
                  universalLinksService.didLaunchAppFromLink({ url })
                }
              }
            }
          } catch (e) {
            console.error(e)
          }
        }

        // Wait for the current state change to complete before re-routing
        $rootScope.$on('firstStateChangeSuccess', () => {
          window.plugins.appsFlyer.initSdk(options, appsFlyerLinkHandler, err => console.error(err))
          window.plugins.appsFlyer.registerOnAppOpenAttribution(appsFlyerLinkHandler, err =>
            console.error(err)
          )
        })

        analyticsServicesArray.push({
          name: 'appsFlyerMobile',
          id: appsFlyerSDK.devKey,
          instance: window.plugins.appsFlyer,
        })
      }

      const oneTrustSDKCategories = oneTrustSDK.enabled
        ? Object.fromEntries(
            analyticsServicesArray.map(({ name }) => {
              const category = oneTrustSDK.categories?.[name] ?? oneTrustSDK.defaultCategory
              return [name, category]
            })
          )
        : {}

      const filteredAnalyticsServicesArray = analyticsServicesArray.filter(({ id }) => id)
      const analyticsService = enterpriseAnalytics.init(
        filteredAnalyticsServicesArray,
        oneTrustSDKCategories,
        $document[0]
      )

      // Log pageviews
      let lastTrackedPage = ''
      service.logPageView = async (page, title, referrer) => {
        /*  To avoid same page views being tracked multiple times on Cordova, we have to cache
         *  and check if the page view is the same as previous one and filter them out.
         */
        if (isMobileApp()) {
          // Remove "/" at the end of the page url
          if (page.charAt(page.length - 1) === '/') {
            page = page.slice(0, -1)
          }

          if (page === lastTrackedPage) {
            return
          }

          lastTrackedPage = page
        }

        const userInfo = await getUserInfoAnalytics()
        const unataAppVersion = config.version
        const unataMobileAppVersion = mobileService.appVersion || ''
        const segmentData = {
          page,
          title,
          referrer,
          userInfo,
          unataAppVersion,
          unataMobileAppVersion,
        }
        return analyticsService.log({
          event: 'pageview',
          data: { page, title, segmentData },
        })
      }

      /* Generic (GA/GTM) and Segment-specific log events
       *  Two separate functions to serve generic events calls for either GA/GTM or
       *  Segment-specific calls. Unfortunately, due to the difference in tracking name values,
       *  this must be separated - along with the fact that only some events are captured in
       *  Segment and vice versa
       */
      service.logEvent = async (category, action, label, value) =>
        analyticsService.log({
          event: 'event',
          data: { category, action, label, value },
        })

      service.logSegmentEvent = async (name, segmentArgs = {}) => {
        const absUrl = $location.absUrl()
        const page = isMobileApp() ? getHtml5Url(absUrl) : absUrl
        const userInfo = await getUserInfoAnalytics()
        const unataAppVersion = config.version
        const unataMobileAppVersion = mobileService.appVersion || ''
        const newArgs = {
          ...segmentArgs,
          unataAppVersion,
          unataMobileAppVersion,
        }
        return analyticsService.log({
          event: 'event',
          data: {
            segmentData: { page, segmentArgs: newArgs, userInfo },
            segmentName: name,
          },
        })
      }

      eventEmitter.on(EVENT_TYPES.LOG_SEGMENT_EVENT, ({ name, segmentArgs }) => {
        service.logSegmentEvent(name, segmentArgs)
      })

      /*
       * Featured Product Impressions
       */
      service.logFeaturedProductImpression = (product, location) => {
        const segmentProduct = service.getProductInfoAnalytics(product)
        const impressionData = {
          impression_id: uuidv4(),
          page_type: location,
        }
        service.featuredProductImpressions[product.id] = impressionData

        const segmentPayload = {
          item_details: segmentProduct,
          sponsored: segmentProduct.sponsored,
          sponsored_source: segmentProduct.sponsored_source,
          ...impressionData,
        }
        return service.logSegmentEvent('store.featured_product_impression', segmentPayload)
      }

      service.setFeaturedProductImpressionToStore = productId => {
        utilService.setExpiringValueToStore(
          FP_IMPRESSION_KEY,
          {
            ...utilService.getExpiringValueFromStore(FP_IMPRESSION_KEY),
            [productId]: service.featuredProductImpressions[productId],
          },
          FP_IMPRESSION_EXPIRATION
        )
      }

      service.getFeaturedProductImpression = productId => {
        return (
          service.featuredProductImpressions[productId] ||
          utilService.getExpiringValueFromStore(FP_IMPRESSION_KEY)?.[productId]
        )
      }

      /*
       * Promoted Placement Impressions
       */
      service.logPlacementImpression = (placements, location, type) => {
        if (!placements?.length) return

        const key = type || placements.route
        service.placementImpressions[key] = {
          impression_id: uuidv4(),
          placement_type: key,
          page_type: location,
        }

        const segmentPayload = {
          ...service.placementImpressions[key],
          ...getBannerSegmentPayload(),
          placement: placements.map((placement, index) => ({
            id: placement.id,
            placement_name: placement.name,
            content_type: placement.media?.type,
            target_action: placement.target?.type,
            position: index,
          })),
        }

        return service.logSegmentEvent('store.placements_impression', segmentPayload)
      }

      eventEmitter.on(EVENT_TYPES.LOG_PLACEMENT_IMPRESSION, ({ placements, location, type }) =>
        service.logPlacementImpression(placements, location, type)
      )

      service.setActivePlacementImpressionToStore = type => {
        service.activePlacementImpression = service.placementImpressions[type]
        utilService.setExpiringValueToStore(
          PLACEMENT_IMPRESSION_KEY,
          service.placementImpressions[type],
          PLACEMENT_IMPRESSION_EXPIRATION
        )
      }

      service.getActivePlacementImpression = () =>
        service.activePlacementImpression ||
        utilService.getExpiringValueFromStore(PLACEMENT_IMPRESSION_KEY)

      /* Log ecommerce events
       *  Most of these accept a location parameter, which is a string identifying the view / section
       *  where the product was located when the event occurred.
       */

      // Log a product being shown to a user, generally either in a product tile or a related product cell
      service.logProductImpression = (product, location) => {
        if (product.placement) return service.logFeaturedProductImpression(product, location)

        return analyticsService.log({
          event: 'impression',
          subject: 'product',
          data: { product, location },
        })
      }

      // Log a view of a product details view
      service.logProductDetailsView = product =>
        analyticsService.log({ event: 'detail', subject: 'product', data: { product } })

      // Log clicking on a product, via a product tile or related product cell
      service.logProductClick = (product, location, extData) =>
        analyticsService.log({
          event: 'click',
          subject: 'product',
          data: { product, location, extData },
        })

      // Log adding a product to the cart
      service.logProductAddToCart = (product, location, contextIntent) => {
        const logLocation = appendListOrCartIfInFlyer(location, contextIntent)
        analyticsService.log({
          event: 'add',
          subject: 'product',
          data: { product, logLocation },
        })
      }

      // Log removing a product from the cart
      service.logProductRemoveFromCart = (product, location, contextIntent) => {
        const logLocation = appendListOrCartIfInFlyer(location, contextIntent)
        analyticsService.log({
          event: 'remove',
          subject: 'product',
          data: { product, logLocation },
        })
      }

      // Log an offer being shown to a user, generally either in an offer tile or a related offer cell
      service.logOfferImpression = (offer, location) =>
        analyticsService.log({ event: 'impression', subject: 'offer', data: { offer, location } })

      // Log a view of an offer details view
      service.logOfferDetailsView = offer =>
        analyticsService.log({ event: 'detail', subject: 'offer', data: { offer } })

      // Log clicking on an offer, via coupon clipper or activate button
      service.logOfferClick = (offer, location) =>
        analyticsService.log({ event: 'click', subject: 'offer', data: { offer, location } })

      // Log checkout initiation
      service.logBeginCheckout = async cart => {
        analyticsService.log({
          event: 'checkout',
          subject: 'start',
          data: { cart },
        })
      }

      // Log completion of a checkout step
      service.logCheckoutStep = (step, option, products) =>
        analyticsService.log({
          event: 'checkout',
          subject: 'step',
          data: { step, option, products },
        })

      // Log completion of a checkout option
      service.logCheckoutOption = (step, option) =>
        analyticsService.log({ event: 'checkout', subject: 'option', data: { step, option } })

      // Log completion of checkout
      service.logCheckoutCompletion = order => {
        const orderPayload = { ...order }
        delete orderPayload.billing_address
        delete orderPayload.delivery_address
        delete orderPayload.ext_data

        analyticsService.log({
          event: 'checkout',
          subject: 'completion',
          data: { order: orderPayload },
        })
      }

      // Log viewing recipe detail
      service.logRecipeDetailsView = recipe =>
        analyticsService.log({ event: 'detail', subject: 'recipe', data: { recipe } })

      // Log search results event
      service.logSearch = ({ term, results }) =>
        analyticsService.log({ event: 'search', data: { term, results } })

      // Log user login event
      service.logUserLogin = () => analyticsService.log({ event: 'login' })

      // Segment functions
      service.identify = async () => {
        // TODO: Should this still fire if anonymousId fails?
        const anonymousID = await anonymousIdService.getId()
        if (anonymousID && analyticsService.segment) {
          analyticsService.segment.identify({
            identifiableID: `${env.shortCode}_${auth.user.id}`,
            anonymousID,
          })
        }
      }

      if (config.criteoId) {
        initCriteoAnalytics(oneTrustSDKCategories.criteo)
      }

      prepCriteoAnalytics()

      if (config.elevaate?.enabled && config.elevaate?.tag) {
        initElevaateAnalytics(oneTrustSDKCategories.elevaate)
      }

      // Configure Facebook Mobile SDK
      prepFacebookMobileSDK()

      const getUserInfoAnalytics = () => {
        const deferred = $q.defer()
        anonymousIdService.getId().then(anonymousId => {
          const store = auth.user.store || {}
          store.address = store.address || {}

          const buildObject = token => ({
            is_loyalty_user: !!userService.loyaltyCards.length,
            is_instacart_member: auth.user.ic_linked,
            user_id: `${env.shortCode}_${auth.user.id}`,
            user_status: auth.user.status,
            usersession_id: auth.getSessionId(),
            retailer_code: env.shortCode,
            store_id: store.id,
            store_zip: store.address.postal_code,
            store_city: store.address.city,
            store_country: store.address.country,
            store_province: store.address.province,
            group: {
              retailer_code: env.shortCode,
            },
            shopping_mode: auth.getCurrentShoppingMode(),
            zip_code: auth.user.context && auth.user.context.deliveryPostalCode,
            user_city: auth.user.context && auth.user.context.city,
            user_province:
              auth.user.context && (auth.user.context.province_short || auth.user.context.province),
            trial_id: anonymousId,
            visit_token: token,
          })

          // Send the analytics object even if getting the `visit_token` fails
          enterpriseAnalytics.expiryToken
            .getToken()
            .then(token => deferred.resolve(buildObject(token)))
            .catch(() => deferred.resolve(buildObject()))
        })

        return deferred.promise
      }

      // Cart events are expected to pass the cart to this service.
      service.getCartInfoAnalytics = cart => {
        return {
          cart_items_count: cart.items.length,
          pre_discount_product_total: cart.pre_discount_product_total,
          product_total: cart.product_total,
          cart_route: cart.route,
          tax_total: cart.tax_total,
          cart_total: cart.total,
          credit_total: cart.credit_total,
          delivery_fee: cart.delivery_fee,
          offer_total: cart.offer_total,
          pickup_fee: cart.pickup_fee,
          subscription_total: cart.subscription_total,
          tip_total: cart.tip_total,
          tip_percent: cart.tip_percent,
        }
      }

      service.getProductListAnalytics = (productList, options) => {
        const product_results = []
        productList?.forEach((product, index) => {
          const productAnalytics = service.getProductInfoAnalytics(
            product,
            index,
            null,
            options?.slim
          )
          if (productAnalytics) {
            product_results.push(productAnalytics)
          }
        })
        return product_results
      }

      /**
       * Formats a given recipe list for usage in analytics events
       * @param {Array} recipeList An array of recipes
       * @param {string?} searchID Optionally the search that triggered
       */
      service.getRecipeListAnalytics = (recipeList, searchID) => {
        let formattedEvents = recipeList.map(service.getRecipeAnalytics)
        if (searchID) {
          formattedEvents = formattedEvents.map(r => {
            return {
              ...r,
              snrid: searchID,
            }
          })
        }
        return formattedEvents
      }

      /**
       * Removes special characters that prevent segment to work properly.
       * Any text in Segment payload that contains the character % will
       * cause the event to not trigger
       *
       * @param {string} text
       * @returns {string}
       */
      const _sanitizeAnalyticsText = text => {
        if (!text) return

        return text.replace(/%/g, '')
      }

      const getSalePricing = store_product => {
        const loyaltyPrice = config.enableLoyaltyPricing && store_product.loyalty_price
        const salePrice = !config.enableLoyaltyPricing && store_product.sale_price

        if (loyaltyPrice) {
          const { loyalty_price, loyalty_end_date, loyalty_quantity } = store_product
          return {
            sale_price: loyalty_price,
            sale_end_date: loyalty_end_date,
            sale_quantity: loyalty_quantity,
            type: 'loyalty',
          }
        }
        if (salePrice) {
          const { sale_price, sale_end_date, sale_quantity } = store_product
          return { sale_price, sale_end_date, sale_quantity, type: 'sale' }
        }
        return { sale_price: null, sale_end_date: null, sale_quantity: null, type: null }
      }
      const getSponsoredSource = store_product => {
        if (!store_product.placement) return undefined

        if (!store_product.sponsoredBy) return SEGMENT_SPONSORED_SOURCE.DASHBOARD

        return store_product.sponsoredBy === SEGMENT_SPONSORED_SOURCE.INSTACART
          ? store_product.sponsoredBy
          : SEGMENT_SPONSORED_SOURCE.THIRD_PARTY
      }

      const getProductSnrid = store_product => {
        const snrid = store_product.snrid || searchService.snrid

        // If we're on the search page, include snrid
        if ($state.current.name === 'main.search') return snrid

        // If we were directed to a recipe from the search page, include snrid
        if (
          $state.current.name === 'main.recipes.details' &&
          previousStateName === 'main.search.recipes'
        )
          return snrid

        // Otherwise, do not include snrid
        return null
      }

      service.getProductInfoAnalytics = (product, index, corner_distance, slim = false) => {
        const store_product = product?.store_product || product?.product || product
        if (store_product?.id) {
          // This is a workaround for recipes which returns a tags object instead of an array
          const tags = store_product.tags?.tags || store_product.tags
          const previous_purchase = tags?.includes('purchased') || false

          const slimPayload = {
            name: _sanitizeAnalyticsText(store_product.name),
            id: store_product.id,
            ic_item_id: store_product.ic_item_id,
            is_primary_result: searchService.isPrimaryResult(store_product),
            previous_purchase,
            position: index || (index === 0 ? 0 : null),
            product_type: store_product.product_type,
            sponsored: store_product.placement,
            sponsored_source: getSponsoredSource(store_product),
          }

          if (slim) return slimPayload

          return {
            ...slimPayload,
            brand_name: _sanitizeAnalyticsText(store_product.brand_name),
            ic_product_id: store_product.ic_product_id,
            aisle: store_product.aisle,
            base_price: store_product.base_price,
            product_rating: store_product.product_rating,
            price_alert: store_product.price_alert,
            reco_rating: store_product.reco_rating,
            snrid: getProductSnrid(store_product),
            sale_data: getSalePricing(store_product),
            offers: store_product.offers,
            corner_distance,
            config_type: store_product?.product_config?.config_type,
          }
        }
        return null
      }

      /**
       * Formats a given recipe into a format that's acceptable to analytics; in
       * particular, this sanitizes the recipe name and converts the tags into an
       * array of strings instead of objects (behaviour subject to change to match
       * the categories property)
       */
      service.getRecipeAnalytics = (recipe, index) => {
        let formattedProducts = {}

        if (recipe.products) {
          formattedProducts = Object.keys(recipe.products).reduce((result, category) => {
            result[category] = service.getProductListAnalytics(recipe.products[category])
            return result
          }, {})
        }

        return {
          id: recipe.id,
          name: _sanitizeAnalyticsText(recipe.name),
          categories: recipe.categories,
          category: recipe.categories[0],
          sub_categories: recipe.categories.slice(1),
          saved_count: recipe.saved_count,
          tags: recipe.tags?.tags?.map(tag => tag.tag_name) || [],
          item_details: formattedProducts,
          preparation_information: recipe.preparation_information,
          position: index || (index === 0 ? 0 : null),
        }
      }

      // Logs the checkout CTA button click
      service.logCheckoutCTAClick = (category, value) =>
        analyticsService.log({
          event: 'event',
          data: {
            value,
            category,
            action: 'click',
            name: 'checkout_click',
          },
        })

      function initCriteoAnalytics(oneTrustSDKCategory) {
        ;(function (z) {
          const b = function (x) {
            return document.getElementsByTagName(x)[0]
          }
          const c = b('head') || b('body') || b('script').parentNode
          const d = document.createElement('script')
          d.type = 'text/javascript'
          d.async = 1
          d.src = z
          if (oneTrustSDKCategory) {
            waitOneTrust().then(OneTrust =>
              OneTrust.InsertScript(d.src, 'head', null, null, oneTrustSDKCategory)
            )
          } else {
            c.insertBefore(d, null)
          }
        })(`//dynamic.criteo.com/js/ld/ld.js?a=${config.criteoId}`)
      }

      function initElevaateAnalytics(oneTrustSDKCategory) {
        let scriptSrc = config.elevaate.tag
        if (isMobileApp() && scriptSrc.startsWith('//')) {
          scriptSrc = scriptSrc.replace(/^\/\//, 'https://')
        }
        ;(function (win, doc, tagName, srcPath, p, a, h) {
          win[p] = win[p] || []
          win[p].push({
            event: 'elevaate',
            start: new Date().getTime(),
          })
          p = `?gv=${encodeURIComponent(p)}`
          a = doc.createElement(tagName)
          h = doc.getElementsByTagName(tagName)[0]
          a.async = 1
          a.src = srcPath + p
          if (oneTrustSDKCategory) {
            waitOneTrust().then(OneTrust =>
              OneTrust.InsertScript(a.src, 'body', null, null, oneTrustSDKCategory)
            )
          } else {
            h.parentNode.insertBefore(a, h)
          }
        })(window, document, 'script', scriptSrc, 'elevaateData')
      }

      function prepCriteoAnalytics() {
        const baseCriteoLogConstructor = async events => {
          if (shouldFireAnalytics && window.criteo_q) {
            const retailerVisitorId = await anonymousIdService.getId()

            window.criteo_q.push(
              { event: 'setAccount', account: config.criteoId },
              { event: 'setCustomerId', id: auth?.user?.id },
              { event: 'setEmail', email: auth?.user?.email || '' },
              { event: 'setRetailerVisitorId', id: retailerVisitorId },
              ...events
            )
          }
        }

        /**
         * log Product Detail Page to Criteo
         *
         * @category Criteo // OneTag
         * @param {Object} obj - Params for the logCriteoProductDetailsPage function
         * @param {String} obj.item - The product id for the specific product being viewed. e.g.
         * @param {String} obj.price - The current price for the displayed product e.g 475.00
         * @param {Boolean} obj.availability - 1 if the product is in stock and 0 if it is out of stock
         */
        service.logCriteoProductDetailsPage = ({ item, price, availability }) => {
          baseCriteoLogConstructor([
            {
              event: 'viewItem',
              availability,
              price,
              item,
            },
          ])
        }

        /**
         * log Order Confirmation Page to Criteo
         *
         * @category Criteo // OneTag
         * @param {Object} obj - Params for the logCriteoOrderConfirmation function
         * @param {String} obj.id - The transaction or order ID from your eCommerce system
         * @param {Array} obj.items - The list of child product ids that are displayed on the cart page. Add a new line for each item in the basket.
         * @param {String} obj.items[0].id - The current price for the displayed product e.g 475.00
         * @param {String} obj.items[0].price - The current price for the displayed product e.g 475.00
         * @param {String} obj.items[0].quantity - The current price for the displayed product e.g 475.00
         */
        service.logCriteoOrderConfirmation = ({ id, orderItems }) => {
          const item = orderItems.map(order => ({
            id: order.ext_id,
            price: order.store_product.sale_price || order.store_product.base_price,
            quantity: order.quantity,
          }))

          baseCriteoLogConstructor([
            {
              event: 'trackTransaction',
              currency: 'USD', // TODO: This will be a big problem in the future if we ever have a CAD retailer on Criteo.
              id,
              item,
            },
          ])
        }

        service.logCriteoStoreProducts = ({ filterParams = {}, keyword, page_number, items }) => {
          const filters = []

          if (filterParams.tags) {
            filters.push({
              name: 'tag',
              operator: 'eq',
              value: filterParams.tags.sort().join(', '),
            })
          }

          if (filterParams.brand_names) {
            filters.push({
              name: 'brand',
              operator: 'eq',
              value: filterParams.brand_names.sort().join(', '),
            })
          }

          const category =
            filterParams.categories && filterParams.categories[0]
              ? filterParams.categories[0].join('>')
              : null

          baseCriteoLogConstructor([
            {
              event: keyword ? 'viewSearchResult' : 'viewCategory',
              category: keyword ? undefined : category,
              item: items.slice(0, 100),
              page_number,
              keyword,
              filters,
            },
          ])
        }
      }

      /**
       * Initializes methods for publishing App Events
       * to Facebook Analytics via the Facebook Mobile SDK.
       */
      function prepFacebookMobileSDK() {
        const fbConfig = integrations.facebookMobileSDK

        // Facebook Event Name Constants
        const EVENTS = {
          ADD_TO_CART: 'fb_mobile_add_to_cart',
          COMPLETE_REGISTRATION: 'fb_mobile_complete_registration',
        }

        // Primary flag to ensure app events only get fired within the app
        // and for clients that have the Facebook SDK enabled.
        const allowFBAppEvents =
          fbConfig &&
          fbConfig.enabled &&
          mobileService.isMobileApp() &&
          $window.facebookConnectPlugin

        // Set logging functions to an empty function if allowAppEvents is undefined.
        // This will prevent errors from being thrown.
        const defaultFBFunction = () => {}

        /**
         * Logs a custom event to the Facebook App Events API
         * Refer to the following link for event payload structure:
         * https://developers.facebook.com/docs/marketing-api/app-event-api/v2.8#event_structure
         */
        service.logFBEvent = allowFBAppEvents
          ? (name, params, valueToSum) => {
              $window.facebookConnectPlugin.logEvent(name, params, valueToSum)
            }
          : defaultFBFunction

        /**
         * Logs a "Purchase" event to Facebook.
         * @param {number} value -  the amount that was purchased
         * @param {string} currency - the currency, in ISO-4217 (http://en.wikipedia.org/wiki/ISO_4217)
         */
        service.logFBPurchase = allowFBAppEvents
          ? order => {
              const value = order.estimated_totals.total
              $window.facebookConnectPlugin.logPurchase(value, fbConfig.currency)
            }
          : defaultFBFunction

        /**
         * Logs an "Add to Cart" event to Facebook.
         * Uses the custom event format as the cordova-plugin-facebook4 does not
         * expose a direct method for this particular type of event.
         * @param {object} product - a product object structured as retrieved from the API
         */
        service.logFBAddToCart = allowFBAppEvents
          ? (product, quantity = null) => {
              const price = product.sale_price || product.base_price
              const value = product.order_by_weight
                ? parseFloat(price * quantity).toFixed(2)
                : price

              $window.facebookConnectPlugin.logEvent(
                EVENTS.ADD_TO_CART,
                {
                  fb_currency: fbConfig.currency,
                },
                value
              )
            }
          : defaultFBFunction

        /**
         * Logs a "Registration Complete" event to Facebook.
         */
        service.logFBUserRegistered = allowFBAppEvents
          ? () => {
              $window.facebookConnectPlugin.logEvent(EVENTS.COMPLETE_REGISTRATION, {}, 1)
            }
          : defaultFBFunction
      }

      // Log pageviews
      // Notes:
      // * Because this is a SPA, the document's referrer from the page's initial
      //   load will be retained when this event is fired, regardless of whether
      //   a value has been set or not.
      // * On page's first load, this event will fire having newUrl == oldUrl
      service.onLocationChangeSync = (event, newUrl, oldUrl) => {
        let previousUrl
        if (isMobileApp()) {
          newUrl = getHtml5Url(newUrl)
          oldUrl = getHtml5Url(oldUrl)
        }
        if (!document.referrer && oldUrl === newUrl) {
          previousUrl = ''
        } else {
          // Prioritize using oldUrl when the oldUrl and newUrl received by this
          // event actually differ. Otherwise, we can safely assume the user came
          // from a different external referrer.
          previousUrl = newUrl !== oldUrl ? oldUrl : document.referrer
        }

        if ($rootScope.title && !$rootScope.titlePromise) {
          service.logPageView(newUrl, $rootScope.title, previousUrl)
        } else {
          $rootScope.titlePromise.promise.finally(() => {
            // Once the page title is set, we log the pageview event
            service.logPageView(newUrl, $rootScope.title, previousUrl)
          })
        }
      }

      // Log identify
      $rootScope.$on('auth', (event, action) => {
        if (action === 'userLoggedIn') {
          service.identify()
        }
      })

      const appendListOrCartIfInFlyer = (location, contextIntent) => {
        if (location) {
          if (location.indexOf('Flyerhomepage') === 0 || location.indexOf('Flyer') === 0) {
            // If we're logging a product add to cart event from:
            //  Flyerhomepage - Regular, Flyerhomepage - Preview, Flyer - Regular, or Flyer - Preview,
            //  Log whether or not it's being added to the user's default in-store list, or the user's cart
            const listOrCart = contextIntent === 'instore' ? 'List' : 'Cart'
            return `${location} - ${listOrCart}`
          }
          // Otherwise, return the log location as is
          return location
        }
      }

      // Log navigations away to other domains.
      $window.addEventListener('beforeunload', event => {
        const anchor = event.target.activeElement?.closest('a')
        if (!anchor) {
          return
        }

        const { host, href } = anchor
        if (host !== $location.host()) {
          service.logSegmentEvent('store.navigate_away', { host, href })
        }
      })

      // Log new tab openings.
      $window.addEventListener('click', event => {
        const anchor = event.target.closest('a')
        if (!anchor) {
          return
        }

        const { host, href, target } = anchor
        if (target.split(/\s+/).includes('_blank')) {
          service.logSegmentEvent('store.new_tab', { host, href })
        }
      })
    }
  )

  // Directives to provide easy access to analytics in templates
  //
  // Usage:     <analytics-x="[param1, param2]"/>
  // Will call: analytics.logX(param1, param2)
  //
  // Directive to toggle whether analytics should be sent or not, must be placed with the analytics-click directive
  // If this directive is not specifed it will send the analytics event
  //
  // Usage : <div analytics-click="[param1, param2]" send-analytics="boolean"></div>
  // Result: true will send the analytics, false will NOT send analytics

  .directive('analyticsClick', function () {
    return {
      controller($scope, $element, $attrs, analytics) {
        const sendAnalytics = $scope.$eval($attrs.sendAnalytics)
        if (!_.isNil(sendAnalytics) && !sendAnalytics) {
          return
        }
        $element.bind('click', () => {
          analytics.logEvent.apply(analytics.logEvent, $scope.$eval($attrs.analyticsClick))
        })
      },
    }
  })

  .directive('analyticsProductImpression', function (analytics) {
    return (scope, element, attrs) => {
      const sendAnalytics = scope.$eval(attrs.sendAnalytics)
      if (!_.isNil(sendAnalytics) && !sendAnalytics) {
        return
      }
      analytics.logProductImpression.apply(
        analytics.logProductImpression,
        scope.$eval(attrs.analyticsProductImpression)
      )
    }
  })

  .directive('analyticsProductClick', function (analytics) {
    return (scope, element, attrs) => {
      const sendAnalytics = scope.$eval(attrs.sendAnalytics)
      if (!_.isNil(sendAnalytics) && !sendAnalytics) {
        return
      }
      element.bind('click', () => {
        analytics.logProductClick.apply(
          analytics.logProductClick,
          scope.$eval(attrs.analyticsProductClick)
        )
      })
    }
  })

  .directive('analyticsOfferImpression', function (analytics) {
    return (scope, element, attrs) => {
      const sendAnalytics = scope.$eval(attrs.sendAnalytics)
      if (!_.isNil(sendAnalytics) && !sendAnalytics) {
        return
      }
      analytics.logOfferImpression.apply(
        analytics.logOfferImpression,
        scope.$eval(attrs.analyticsOfferImpression)
      )
    }
  })

  .directive('analyticsOfferClick', function (analytics) {
    return (scope, element, attrs) => {
      const sendAnalytics = scope.$eval(attrs.sendAnalytics)
      if (!_.isNil(sendAnalytics) && !sendAnalytics) {
        return
      }
      element.bind('click', () => {
        analytics.logOfferClick.apply(
          analytics.logOfferClick,
          scope.$eval(attrs.analyticsOfferClick)
        )
      })
    }
  })

  .directive('analyticsPlacementImpression', function (analytics) {
    return (scope, element, attrs) => {
      const sendAnalytics = scope.$eval(attrs.sendAnalytics)
      if (!_.isNil(sendAnalytics) && !sendAnalytics) {
        return
      }
      analytics.logPlacementImpression.apply(
        analytics.logPlacementImpression,
        scope.$eval(attrs.analyticsPlacementImpression)
      )
    }
  })
