import angular from 'angular'
import template from './icExpressPopover.html'

class IcExpressPopover {
  constructor(config, lang, utilService) {
    Object.assign(this, {
      config,
      lang,
      utilService,
    })
  }

  $onInit() {
    const {
      image,
      membershipTitle,
      subtitle,
      primaryButtonLabel,
      secondaryButtonLabel,
    } = this.config.icExpress.placements.popover

    this.image = this.image || image
    this.title = this.title || membershipTitle
    this.subtitle = this.subtitle || subtitle
    this.primaryButtonLabel = this.primaryButtonLabel || primaryButtonLabel
    this.secondaryButtonLabel = this.secondaryButtonLabel || secondaryButtonLabel

    const el = document.querySelector('.ic-express-popover')

    if (el) {
      this.utilService.trapFocusOn(el)
      el.focus()
    }
  }

  primaryClick() {
    this.onPrimaryButtonClicked()
  }

  secondaryClick() {
    this.onSecondaryButtonClicked()
  }
}

export default angular.module('IcExpressPopoverComponentModule', []).component('icExpressPopover', {
  bindings: {
    image: '@',
    title: '<',
    subtitle: '@',
    primaryButtonLabel: '@',
    secondaryButtonLabel: '@',
    onPrimaryButtonClicked: '&',
    onSecondaryButtonClicked: '&',
    enableRetailerTheme: '<',
  },
  template,
  controller: IcExpressPopover,
})
