import React, { useEffect } from 'react'
import { config as configService } from '@instacart/enterprise-services'
import { useLoading, useSubscriptions } from './hooks'
import SubscriptionCheckout from './SubscriptionCheckout'

export { default as CartCheckoutSubscriptionSelect } from './CartCheckoutSubscriptionSelectSmart'
export { SUBSCRIPTION_STEPS } from './SubscriptionCheckout'
export { SUBSCRIPTION_PATHS } from './constants'

export default props => {
  // Subscribe to the services we need
  const config = configService.selectors.getConfig()
  const loadingService = useLoading()
  const subscriptionsService = useSubscriptions()

  // Destructure some objects for ease of use
  const { isLoaded } = loadingService.selectors
  const { GET_STORE_SUBSCRIPTIONS } = subscriptionsService.constants

  // Check if we need to fetch data
  useEffect(() => {
    if (!isLoaded(GET_STORE_SUBSCRIPTIONS)) {
      subscriptionsService.actions.getStoreSubscriptions()
    }
  }, [GET_STORE_SUBSCRIPTIONS, isLoaded, subscriptionsService.actions])

  return (
    <SubscriptionCheckout
      {...props}
      services={{ loadingService, subscriptionsService }}
      config={config}
      i18n={config.lang.subscriptions}
    />
  )
}
