import angular from 'angular'

export default angular.module('FeaturedHeaderComponentModule', []).component('featuredHeader', {
  bindings: {
    collection: '<',
    hideBack: '<',
  },
  controller(mobileService, lang) {
    this.lang = lang
    const { isMobile } = mobileService
    const { images } = this.collection

    this.showBackButton = isMobile && !this.hideBack
    this.showCollectionName = !!(this.collection.show_name_with_details && this.collection.name)
    // We want to show a translucent dark overlay on top of the background image if there is text (or a back button for mobile)
    this.headerHasText = this.showCollectionName || this.collection.description
    const imageObj = isMobile && images.mobile ? images.mobile : images.desktop

    if (imageObj && typeof imageObj === 'object') {
      this.imageSrc = imageObj.src
      this.imageAltText = imageObj.alt_text
    } else {
      this.imageSrc = imageObj
      this.imageAltText = ''
    }

    // TODO: Incorporate proper back button behaviour (currently in pageHeader directive) in one place and apply it to both
    //       pageHeader and featuredHeader directives. Currently featuredHeader back button always just sends users to 'main.home'.
  },
  template: `
      <section class="featured-header">
        <div class="background-image" ng-class=":: { 'header-has-text': $ctrl.headerHasText }" >
          <img src="{{ $ctrl.imageSrc }}" alt="{{ $ctrl.imageAltText }}" />
        </div>
        <a ng-if=":: $ctrl.showBackButton" ui-sref="main.home" class="mobile-back-button" aria-label="{{::lang.aria.goBack}}"></a>
        <h2 ng-if=":: $ctrl.showCollectionName" class="title">{{ $ctrl.collection.name }}</h2>
        <div class="description">{{ $ctrl.collection.description }}</div>
      </section>
    `,
})
