import angular from 'angular'

export default angular.module('NavLogoutButtonModule', []).component('unataNavLogoutButton', {
  bindings: {
    parent: '<',
  },
  controller(auth, config, lang, mobileNavService, registrationService, uibModalAriaLabelService) {
    this.auth = auth
    this.lang = lang
    this.registrationService = registrationService
    this.config = config
    this.uibModalAriaLabelService = uibModalAriaLabelService

    this.hideSideNav = () => mobileNavService.hideSlideOutMenu()
  },
  template: `
    <div class="nav-menu login">
      <ul>
        <li ng-if="$ctrl.auth.user.status === 'trial'">
          <button id="nav-register-btn" data-test="nav-login" ng-class="{'center-text': !$ctrl.config.dotComModule.mobileNav.loginButton.showIcons}" ng-click="$ctrl.registrationService.open('login'); $ctrl.uibModalAriaLabelService.setLabel($ctrl.lang.values.login)" analytics-click="['nav login register button', 'click']" refocus>
            <span ng-class="{'icon-login': $ctrl.config.dotComModule.mobileNav.loginButton.showIcons}" aria-hidden="true"></span>
            {{::$ctrl.lang.values.loginOrRegister}}
          </button>
        </li>
        <li ng-if="$ctrl.auth.user.status !== 'trial'">
          <button id="nav-logout-btn" data-test="nav-logout" ng-class="{'center-text': !$ctrl.config.dotComModule.mobileNav.loginButton.showIcons}" ng-click="$ctrl.hideSideNav(); $ctrl.auth.logout()" analytics-click="['nav user dropdown logout link', 'click']">
            <span ng-class="{'icon-logout': $ctrl.config.dotComModule.mobileNav.loginButton.showIcons}" aria-hidden="true"></span>
            {{::$ctrl.lang.values.logout}}
          </button>
        </li>
      </ul>
    </div>
    `,
})
