import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('FacetedSearchCategoriesComponentModule', [])
  .component('facetedSearchCategories', {
    bindings: {
      searchContext: '<?',
      categories: '<',
      onCategoryClick: '&',
      selectedCategory: '<',
    },

    controller(catalogCategoriesService, config, recipeCategoriesService, mobileService) {
      this.ICSearchEnabled = config.enableICSearch

      switch (this.searchContext) {
        case 'recipes':
          this.categoryNameFromID = id => {
            return recipeCategoriesService.getCategoryFromId(id?.toString())?.name || ''
          }
          break
        default:
          this.categoryNameFromID = id =>
            catalogCategoriesService.categoryFromId(id?.toString())?.name || ''
      }

      this.facetCountFromID = id =>
        _.find(this.categories, category => {
          return category.id === id
        }).item_count || 0

      this.displayClassName = id => {
        // Set the appropriate class names for each line item  (mobile v desktop, current v active)
        // TODO: would be nicer if we could unify this in our styles
        let className = mobileService.isMobile ? 'search-scroll-tab' : 'child'
        // eslint-disable-next-line eqeqeq
        if (mobileService.isMobile && this.selectedCategory == id) {
          className = `${className} current`
          return className
        }
        // eslint-disable-next-line eqeqeq
        if (!mobileService.isMobile && this.selectedCategory == id) {
          className = `${className} active`
          return className
        }
        return className
      }
    },
    template: `
      <ul>
        <li ng-repeat="searchCat in $ctrl.categories" ng-class="$ctrl.displayClassName(searchCat.id)"  class="sidenav-item child">
          <button
            type="button"
            class="text-left"
            ng-click="$ctrl.onCategoryClick({ id: searchCat.id })"
            analytics-click="['search menu ' + $ctrl.categoryNameFromID(searchCat.id) + ' link', 'click']"
            data-test="department-faceted-search-link"
            aria-current="{{ $ctrl.selectedCategory == searchCat.id }}"
          >
            {{$ctrl.categoryNameFromID(searchCat.id)}}
            <span ng-if="::!$ctrl.ICSearchEnabled">({{$ctrl.facetCountFromID(searchCat.id)}})</span>
          </button>
        </li>
      </ul>
    `,
  })
