import _ from 'lodash'
import angular from 'angular'
import tagHelper from '@app/angular/reactComponents/checkbox/tagHelper'
import template from './facetedSearchCheckbox.html'

export default angular
  .module('FacetedSearchCheckboxModule', [])
  .component('facetedSearchCheckbox', {
    template,
    bindings: {
      checked: '<',
      id: '<',
      item: '<',
      label: '<',
      selectValue: '<',
      selectTag: '<',
      selectedTags: '<',
    },
    controller($scope, $state, $stateParams, facetedSearchService, tagsService, utilService) {
      const vm = this

      const selectTagFilter = facetGroupDisplayName => {
        const selectedTags = []

        _.each(vm.selectedTags, (selected, tag) => {
          if (selected) {
            selectedTags.push(tag)
          }

          const ignoredGroupTags = [
            'purchased',
            'on_sale',
            'has_coupon',
            'has_points',
            'loyalty_deal',
          ]

          facetedSearchService.setTagCrumbs(selected, tag, 'tags')
          if (ignoredGroupTags.indexOf(tag) === -1) {
            facetedSearchService.setGroupDisplayName(selected, facetGroupDisplayName)
          }
        })

        tagsService.checkRegistrationRequired({
          activeTags: vm.selectedTags,
          allTags: tagsService.tags(),
          success: () => {
            $state.go('.', {
              tags: selectedTags,
              page: null,
              group_display_name: facetedSearchService.tagGroupDisplayNameArray,
              toggledFilterId: $scope.createId,
            })
          },
          failure: () => {
            $scope.checked = false
            vm.selectedTags[vm.selectValue] = false
          },
        })
      }

      // Create values to pass to react component
      $scope.checked = !!vm.checked || false
      $scope.label = vm.label

      $scope.createId = `tag-${vm.selectValue || ''}`
      $scope.className = `tag ${vm.selectValue || ''}`.trim()

      const tagFixes = tagHelper(vm.item)
      $scope.iconClass = vm.item.icon ? tagFixes.iconClass : null
      $scope.styles = tagFixes.styles

      if (vm.id) {
        $scope.createId += `-${vm.id}`
      }

      $scope.onCheckboxChange = () => {
        $scope.checked = !$scope.checked
        vm.selectedTags[vm.selectValue] = $scope.checked
        selectTagFilter(vm.selectValue)

        if ($scope.checked) {
          // focus on previous checked
          utilService.focusNodeWhenInDOM(`#${$scope.createId}`)
        } else {
          // focus on first element, sometimes when unchecked, it is not visible
          utilService.focusNodeWhenInDOM(`.faceted-filters-wrapper input`)
        }
      }
    },
  })
