import angular from 'angular'

export default angular
  .module('ProductDetailsModalControllerModule', [])
  .controller(
    'ProductDetailsModalController',
    function (
      $controller,
      $scope,
      $state,
      Restangular,
      auth,
      cartService,
      mobileService,
      modalOptions,
      noticeAlert,
      placement,
      productId
    ) {
      Restangular.one('store_products', productId)
        .get()
        .then(product => {
          $controller('ProductDetailsController', {
            $scope,
            productId,
            product,
            placement,
          })
        })
        .catch(responseOrException => {
          if (responseOrException.status === 404) {
            $state.go('main.404')
          } else if (responseOrException instanceof Error) {
            return Promise.reject(responseOrException)
          } else {
            console.error('ProductDetailsModalController - unhandled case', responseOrException)
          }
        })

      $scope.noticeAlert = noticeAlert
      $scope.isModal = true
      $scope.auth = auth
      $scope.cartService = cartService
      $scope.mobile = mobileService

      if (modalOptions) {
        Object.assign($scope, modalOptions)
      }
    }
  )
  .run($templateCache => {
    $templateCache.put('productDetails.html', require('../../productDetails/productDetails.html'))
  })
