import _ from 'lodash'
import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { store } from '@app/services/store'
import { setShoppingMode } from '@app/services/shoppingContext/actions'
import { ShoppingMode } from '@app/types/config'

export default angular
  .module('eComLightServiceModule', [])
  .service('eComLightService', function eComLightService($rootScope, $stateParams, auth, config) {
    this.requiresRedirect = false

    this.setMode = value => {
      store.dispatch(setShoppingMode(value ? ShoppingMode.Catering : ShoppingMode.InStore))
      this.inEComLightMode = value
    }

    this.enableEComLight = () => {
      if (config.eComLight?.enabled && auth?.user?.context) {
        const { intent, shopping_mode } = auth.user.context

        const enableEComLight =
          intent === config.eComLight.fulfillmentType &&
          (shopping_mode === SHOPPING_MODES.CATERING || !config.shoppingSelector.catering?.exists)

        this.setMode(enableEComLight)

        // Sets the appropriate icon to show for the header link to/from e-com light mode
        this.eComLightLinkIcon =
          this.inEComLightMode && config.shoppingSelector[SHOPPING_MODES.INSTORE_LIST].exists
            ? 'icon-list'
            : this.inEComLightMode && config.shoppingSelector[SHOPPING_MODES.PICKUP].exists
            ? 'icon-pickup'
            : `icon-catering`
      }
    }

    this.isNonEComLightCategory = category => {
      return config.eComLight.enabled && config.isDedicatedCateringExperienceSupported
        ? category.id !== config.eComLight?.homeRedirect?.params?.category_id
        : true
    }

    if (!config.eComLight.enabled) {
      this.setMode(false)
    } else {
      this.eComLightCategory = config.eComLight?.homeRedirect?.params?.category_id

      const fetchAllChildrenCategoryIds = category => {
        let categoryIds = [category.id]
        if (category.children && category.children.length > 0) {
          const childrenIds = _.flatMap(category.children, childCategory =>
            fetchAllChildrenCategoryIds(childCategory)
          )
          categoryIds = categoryIds.concat(childrenIds)
        }
        return categoryIds
      }

      // Set the only categories that we want to show users in eCom Light mode
      this.setEComLightCategories = eComLightCategory => {
        if (eComLightCategory) {
          this.eComLightCategoryIds = fetchAllChildrenCategoryIds(eComLightCategory)
        }
      }

      // Pages we don't want to show in eCom Light mode
      const nonEComLightStates = [
        'main.home',
        'main.shop',
        'main.shop.categories',
        'main.shop.flyer.collection',
      ]

      // Returns boolean indicating if the state and params given indicate a state that the user cannot enter in eCom Light mode
      this.isForbiddenState = (state, stateParams) => {
        // Check if the state in question is a category page, and if that category is not part of the list we can show in eCom Light mode
        let isNonEComLightCategory
        // We can only check for isNonEComLightCategory if the categories have been loaded from the API and set through `setEComLightCategories`
        if (
          this.eComLightCategoryIds &&
          state === 'main.shop.category' &&
          stateParams.category_id
        ) {
          isNonEComLightCategory = !_.includes(this.eComLightCategoryIds, stateParams.category_id)
        }

        // Check if the state in question is one of the pages we cannot show users in eCom Light mode
        const isNonEComLightState = _.includes(nonEComLightStates, state)

        return isNonEComLightCategory || isNonEComLightState
      }

      const initializeInEComLightMode = () => {
        const previousState = this.inEComLightMode

        this.enableEComLight()

        // Handles the scenario where eComLight is turned to true after one-time bindings first evaluation
        if (previousState !== this.inEComLightMode && this.inEComLightMode) {
          $rootScope.reloadState()
        }
      }

      initializeInEComLightMode()

      $rootScope.$on('auth', (event, action) => {
        if (action === 'contextUpdated' || action === 'userUpdated') {
          this.enableEComLight()
        }
      })
    }
  })
