import angular from 'angular'

export default angular
  .module('masterCartServicesModule', [])
  .run(($rootScope, cartService, master) => {
    // Listen for cart message from other windows
    master.addMessageListener((msg, event) => {
      if (msg) {
        if (msg.module === 'cartService') {
          switch (msg.method) {
            case 'getItem':
              msg.method += '.result'
              msg.result = cartService.getItem(msg.args[0])
              master.postMessage(msg, event.source)
              break
            case 'addItem':
              cartService.addItem(msg.args[0], msg.args[1])
              break
            case 'removeItem':
              cartService.removeItem(msg.args[0])
              break
            case 'fetch': {
              let { cart } = cartService
              if (typeof cart.plain === 'function') {
                cart = cart.plain()
              }
              msg.method += '.result'
              msg.result = cart
              master.postMessage(msg, event.source)
              break
            }
          }
        }
      }
    })

    // When items are added/removed from cart, notify slave windows
    $rootScope.$on('cartService', (event, action, item, quantity) => {
      switch (action) {
        case 'add':
          master.postMessage({
            module: 'cartService',
            method: 'addItem',
            args: [item.store_product, quantity],
          })
          break

        case 'remove':
          master.postMessage({
            module: 'cartService',
            method: 'removeItem',
            args: [item.store_product],
          })
          break
      }
    })
  })
