// decoratorTarget.initializer is a special function used to construct an instance of the decorated function.
// Think of it as a "meta function"

export const REFLECTION = {
  DECORATORS: {
    /**
     * Accepts a decorator target if the target is a function, and wraps the original function in your own function
     * You can access the original function inside your wrapped function like so: `this.originalFn`
     * For example: `return this.originalFn.apply(this, arguments);` to do a normal call!
     *
     * @param decoratorTarget {object}    - The target function field definition as is passed by the decorator mechanism
     * @param wrappingFn      {function}  - The function that will wrap the original function
     *
     * @example
      export const MyDecorator = (...decoratorArgs) => {
        return (targetFn) => {
          REFLECTION.DECORATORS.wrapOriginalFn(targetFn, function myWrapper(...expectArgs) {
            // Do something custom

            // Call the original function (if wanted)
            return this.originalFn.apply(this, arguments);
          });
        };
      };
     *
     *
     */
    wrapOriginalFn: (decoratorTarget, wrappingFn) => {
      const _initializer = decoratorTarget?.initializer

      if (!_initializer) {
        throw new Error('Unexpected decorator definition: Missing target or initializer!')
      }

      decoratorTarget.initializer = function () {
        const originalFn = _initializer.apply(this, arguments)

        if (!originalFn || typeof originalFn !== 'function') {
          throw new Error(
            'Unexpected decorator definition: Was expecting a function to be decorated!'
          )
        }

        return (...args) => {
          const that = {
            callOriginalFn: (customThis = this, customArgs = args) =>
              originalFn.apply(customThis, customArgs),
            originalThis: this,
          }
          return wrappingFn.apply(that, args)
        }
      }
    },
  },
}
