import angular from 'angular'
import template from '../../components/shared/promoCodeModal/promoCodeModal.html'

export default angular
  .module('promoCodeServicesModule', [])
  .service('promoCodeService', function promoCodeService($uibModal, uibModalAriaLabelService) {
    const service = this

    service.successMessage = ''

    service.setSuccessMessage = message => {
      this.successMessage = message
    }

    service.clearSuccessMessage = () => {
      this.successMessage = ''
    }

    /**
     * Opens a promo code modal dialog.
     * */
    this.open = (options = {}) => {
      const modalInstance = $uibModal.open({
        resolve: {
          ariaLabel: () => {
            uibModalAriaLabelService.setLabel('Promo Code')
          },
          promoCodeOptions() {
            return { ...options, setSuccessMessage: service.setSuccessMessage }
          },
        },
        controller: 'PromoCodeModalController',
        template,
      })
      return modalInstance.result
    }
  })
