/* eslint-disable no-console */

import angular from 'angular'

/*
 * See here for new checkout integration status:
 * https://instacart.atlassian.net/wiki/spaces/ENG/pages/995098979/Payment+Providers+and+new+checkout+styling
 */
import Authorize from './authorize.net'
import Braintree from './braintree'
import FirstData from './firstData/v1'
import FirstDataV2 from './firstData/v2'
import Gigya from './gigya'
import Stripe from './stripe'
import Vantiv from './vantiv'
import Wegpay from './wegpay'

import { getBuildConfig } from '../../rootConfig'

const OPTIONAL_COMPONENTS = {
  Authorize: Authorize.name,
  Braintree: Braintree.name,
  FirstData: FirstData.name,
  FirstDataV2: FirstDataV2.name,
  Gigya: Gigya.name,
  Stripe: Stripe.name,
  Vantiv: Vantiv.name,
  Wegpay: Wegpay.name,
}

const FEATURE_SUPPORT = {
  Authorize: {},
  Braintree: {},
  FirstData: {},
  FirstDataV2: {},
  Gigya: {},
  Stripe: {},
  Vantiv: { googleRecaptcha: true },
  Wegpay: {},
}

const buildConfig = getBuildConfig()

const exportableModules = buildConfig.optionalComponents
  .map(name => OPTIONAL_COMPONENTS[name])
  .filter(Boolean)

const foundPaymentFeatureMaps = buildConfig.optionalComponents
  .map(name => FEATURE_SUPPORT[name])
  .filter(Boolean)

export default angular.module('app.optionalComponents', exportableModules).run(config => {
  const optionalComponents = config.optionalComponents || []
  const original = buildConfig.optionalComponents || []

  if (optionalComponents.join(',') !== original.join(',')) {
    _grabAttentionInConsole(`
      DO NOT USE "optionalComponents" IN THE DASHBOARD - IT WILL NOT WORK!
    `)
  }

  if (
    config.googleRecaptcha?.paymentsEnabled &&
    !foundPaymentFeatureMaps.every(featureMap => Boolean(featureMap.googleRecaptcha))
  ) {
    _grabAttentionInConsole(`
      GOOGLE RECAPTCHA IS SWITCHED ON FOR PAYMENTS BUT NOT ALL PAYMENT
      PROVIDERS LISTED IN "optionalComponents" SUPPORT RECAPTCHA!
    `)
  }
})

function _grabAttentionInConsole(errorMessage) {
  console.log('%cSTOP:', '-webkit-text-stroke: 2px red; color: black; font-size: 50px;')
  console.log(`%c${errorMessage}`, 'color: red; font-size: 14px;')
}
