import angular from 'angular'

export default angular.module('BannerImageComponentModule', []).component('bannerImage', {
  bindings: {
    bannerSlug: '<',
  },
  controller(utilService) {
    Object.assign(this, { utilService })
  },
  template: `
      <img class="banner-image" ng-src="{{$ctrl.utilService.getLogoFromBannerSlug($ctrl.bannerSlug)}}" alt="{{::ctrl.bannerSlug}}">
    `,
})
