import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('collectionsCategoriesServiceComponentModule', [])
  .service(
    'collectionsCategoriesService',
    function collectionsCategoriesService($q, $rootScope, $stateParams, Restangular) {
      const service = this

      $rootScope.$on('reloadState', () => {
        delete service.categories
      })

      // Fetch collections

      service.fetch = function (refresh) {
        const deferred = $q.defer()

        if (!refresh && service.categories) {
          deferred.resolve(service.categories)
          return deferred.promise
        }

        Restangular.all('placements/shop_collections')
          .getList()
          .then(
            items => {
              const results = []

              _.each(items, item => {
                results.push({
                  name: item.target.collection.name,
                  state: 'main.shop.collection',
                  params: {
                    collection_id: item.target.collection.id,
                  },
                  // FIXME: Add tags / sorting when backend supports them
                })
              })

              service.categories = results
              deferred.resolve(service.categories)
            },
            () => {
              // Encountered an error, shop_collections placement may not be set up

              service.categories = []
              deferred.resolve(service.categories)
            }
          )

        return deferred.promise
      }

      // From a set of featured department collections, build collection categories
      service.buildFeaturedDepartmentCollectionCategories = collections => {
        const results = []

        collections.forEach(collection => {
          results.push({
            name: collection.name,
            state: 'main.shop.departments.featured',
            params: {
              collection_id: collection.id,
              category_id: collection.featuredDepartmentID,
            },
          })
        })

        return results
      }
    }
  )
