import { box, randomBytes } from 'tweetnacl'
import naclUtil from 'tweetnacl-util'

function toUTF8Array(str) {
  const utf8 = []
  for (let i = 0; i < str.length; i += 1) {
    let charcode = str.charCodeAt(i)
    if (charcode < 0x80) utf8.push(charcode)
    else if (charcode < 0x800) {
      utf8.push(0xc0 | (charcode >> 6), 0x80 | (charcode & 0x3f))
    } else if (charcode < 0xd800 || charcode >= 0xe000) {
      utf8.push(0xe0 | (charcode >> 12), 0x80 | ((charcode >> 6) & 0x3f), 0x80 | (charcode & 0x3f))
    }
    // surrogate pair
    else {
      i += 1
      // UTF-16 encodes 0x10000-0x10FFFF by
      // subtracting 0x10000 and splitting the
      // 20 bits of 0x0-0xFFFFF into two halves
      charcode = 0x10000 + (((charcode & 0x3ff) << 10) | (str.charCodeAt(i) & 0x3ff))
      utf8.push(
        0xf0 | (charcode >> 18),
        0x80 | ((charcode >> 12) & 0x3f),
        0x80 | ((charcode >> 6) & 0x3f),
        0x80 | (charcode & 0x3f)
      )
    }
  }
  const u8 = new Uint8Array(utf8.length)
  utf8.forEach((charCode, index) => (u8[index] = charCode))

  return u8
}

function convertUint8ArrayToBinaryString(u8Array) {
  let i
  const len = u8Array.length
  let b_str = ''
  for (i = 0; i < len; i += 1) {
    b_str += String.fromCharCode(u8Array[i])
  }
  return b_str
}

function convertBinaryStringToUint8Array(bStr) {
  let i
  const len = bStr.length
  const u8_array = new Uint8Array(len)
  for (i = 0; i < len; i += 1) {
    u8_array[i] = bStr.charCodeAt(i)
  }
  return u8_array
}

const encryptorFactory = () => {
  const keyPair = box.keyPair()
  const publicKeyBinaryString = convertUint8ArrayToBinaryString(keyPair.publicKey)

  class Encryptor {
    constructor() {
      this.externalKey = undefined
    }

    get publicKey() {
      // Base64 encoded public key
      return btoa(publicKeyBinaryString)
    }

    setExternalKey(externalBase64BinaryStringPublicKey) {
      const externalBinaryStringPublicKey = atob(externalBase64BinaryStringPublicKey)
      this.externalKey = convertBinaryStringToUint8Array(externalBinaryStringPublicKey)
    }

    decrypt(message) {
      const formattedValidBase64Message = message.replace(/_/g, '/').replace(/-/g, '+')
      const combinedMessageNonce = atob(formattedValidBase64Message)
      const u8Nonce = convertBinaryStringToUint8Array(combinedMessageNonce.slice(0, 24))
      const u8Message = convertBinaryStringToUint8Array(combinedMessageNonce).slice(24)
      const uint8Message = box.open(u8Message, u8Nonce, this.externalKey, keyPair.secretKey)
      return naclUtil.encodeUTF8(uint8Message)
    }

    encrypt(message) {
      const u8Nonce = randomBytes(box.nonceLength)
      const u8Message = toUTF8Array(message)
      const u8EncryptedMessage = box(u8Message, u8Nonce, this.externalKey, keyPair.secretKey)
      const binaryNonce = convertUint8ArrayToBinaryString(u8Nonce)
      const binaryMessage = convertUint8ArrayToBinaryString(u8EncryptedMessage)
      return btoa(binaryNonce + binaryMessage)
    }
  }

  return Encryptor
}

export default encryptorFactory
