import angular from 'angular'
import { EventType, UnataComm } from '@instacart/enterprise-services-unata-comm'

/**
 * Make Unata Comm globally available for retailer to communicate with our platform.
 * This replaces the need for retailers to include UnataCommSimple.js in their
 * headerSync scripts.
 */
window.UnataComm = UnataComm
window.EventType = EventType

export default angular
  .module('unataCommServicesModule', [])
  .service('unataCommService', function unataCommService($rootScope) {
    const service = this
    this.comm = new UnataComm()

    /**
     * Send a window message.
     * @property {UnataEvent} unataEvent   - The event and the payload to send
     */
    service.send = (...params) => this.comm.send(...params)

    /**
     * Creates an event listener for the desired Unata event.
     *
     * @property {string} eventType   - The desired event to be listened for
     * @property {function} callback  - A function to be executed when the event is observed
     * @return {void}
     */
    service.listen = (...params) => this.comm.listen(...params)

    /**
     * All the possible event types that the widgets fires or watches for.
     */
    service.EventType = EventType

    /**
     * This clears global cached listeners for one-time events. Use this method
     * to reset the listener cache and force them to be fired again the next
     * time they occur. This is useful in situations like if the dotcom header
     * unmounts then later re-mounts and expects the one-time header loaded
     * event to fire again.
     */
    service.clearGlobalCachedListeners = () => this.comm.clearGlobalCachedListeners()

    /**
     * When the app state is reloaded we need to clear all cache single
     * subscription event listeners as references in their wrapped callbacks
     * may be out of date.
     */
    $rootScope.$on('reloadState', () => {
      this.comm.clearGlobalCachedListeners()
    })
  })
