import _ from 'lodash'
import angular from 'angular'
import { MOBILE_HEADER_CLICKED } from '@app/angular/constants/navigation'
import { CHECKOUT_ROUTE_PREFIXES } from '@app/angular/constants/checkout'

export default angular
  .module('MobileNavControllerModule', [])
  .controller(
    'MobileNavController',
    function (
      $document,
      $rootScope,
      $scope,
      $state,
      auth,
      cartService,
      config,
      mobileNavService,
      mobileService,
      utilService
    ) {
      $scope.searching = false
      $scope.cartService = cartService
      $scope.utilService = utilService
      $scope.config = config
      $scope.hideSearch = false
      $scope.auth = auth
      $scope.mobileNavService = mobileNavService
      $scope.mobileService = mobileService
      $scope.showSearchFromClick = false
      $scope.useCustomSearchBar =
        $scope.config.dotComModule.useCustomMobileSearchBar &&
        !($scope.mobileService.isMobileApp() && $scope.config.forceDefaultHeaderOnMobileApp)
      $scope.forceDefaultSearchBar =
        ($scope.mobileService.isMobileApp() && config.forceDefaultMobileSearchOnMobileApp) ||
        (!$scope.mobileService.isMobileApp() && config.forceDefaultMobileSearchOnMobileWeb)
      $scope.forceDefaultHeaderOnMobileWeb =
        !$scope.mobileService.isMobileApp() &&
        $scope.mobileService.isMobile &&
        config.forceDefaultHeaderOnMobileWeb
      $scope.dotComHeaderSyncEnabled =
        $scope.config.dotComModule.enableHeaderFooterSync && !$scope.headerSyncError
      $scope.customHeaderIsLoading =
        ($scope.dotComHeaderSyncEnabled || $scope.dotComHeaderIsLoading) &&
        $scope.useCustomSearchBar

      if (!$scope.cartService.cart) {
        cartService.fetch()
      }

      $scope.$watch('cartService.lastUpdated', (newValue, oldValue) => {
        if (newValue !== oldValue) {
          $scope.categorizedProducts = cartService.getAllStoreProductsByCategory()
        }
      })
      $scope.categorizedProducts = cartService.getAllStoreProductsByCategory()

      $scope.goBack = event => {
        event.preventDefault()
        event.stopPropagation()
        if (cartService.isSubstitutionNotesOpen) {
          cartService.isSubstitutionNotesOpen = false
          $rootScope.$broadcast('mobileDiscardSubsNotes')
          // Needed to make the back button follow the checkout flow
          // and not the browser history
        } else if ($state.current.url === '/v2/cart') {
          $state.go('main.home')
        } else {
          utilService.goBack()
        }
      }

      function setInPageChecks() {
        $scope.inInstoreListPage = $state.current.name === 'main.inStoreList.review'
        $scope.inCheckoutCartPage = ['main.checkout.cart', 'main.checkout.cartV2'].includes(
          $state.current.name
        )
        $scope.inCheckoutConfirmationPage = $state.current.name === 'main.checkout.confirmation'
        $scope.inCheckoutSection = $state.current.name.includes('main.checkout')
        $scope.inNewCheckoutSection = $state.current.url.includes(CHECKOUT_ROUTE_PREFIXES.V2)
        $scope.inListOrCartPage = $scope.inInstoreListPage || $scope.inCheckoutCartPage
        $scope.inDedicatedCheckoutFlow = $scope.inCheckoutSection && !$scope.inCheckoutCartPage
        $scope.showAltNavBar = $scope.inInstoreListPage || $scope.inCheckoutSection
        // For all practical purposes this is used to show the search bar
        // and since the order confirmation page should show the search bar it should not be included here
        $scope.inCheckoutOrList =
          ($scope.inListOrCartPage || $scope.inCheckoutSection) &&
          !$scope.inCheckoutConfirmationPage
      }
      $scope.$on('$stateChangeSuccess', () => {
        setInPageChecks()
        $scope.hideSearch = $scope.inCheckoutOrList
        $scope.showSearchFromClick = false
      })
      setInPageChecks()

      $scope.$on('$stateChangeStart', () => {
        $scope.setSearchingTo(false)
        $scope.hideSearch = false
      })

      $scope.$watch('hideSearch', () => $scope.$broadcast('hideSearch', $scope.hideSearch))

      // TODO: MOVE THIS STUFF TO A MOBILE NAV DIRECTIVE
      // Hide search on scroll
      const mobileNavSearchBarFunctionality = _.throttle(() => {
        $scope.$apply(() => {
          const el = document.querySelector('[ui-view="content"]')
          if (el.scrollTop >= 60 && !$scope.searching) {
            $scope.hideSearch = true
            if (!$scope.inListOrCartPage) {
              $scope.hideLoyaltyCard = true
            }
          } else if ($scope.searching && !$scope.inListOrCartPage) {
            $scope.hideLoyaltyCard = true
          } else {
            $scope.hideSearch = false
            $scope.hideLoyaltyCard = false
          }
        })
      }, 100)

      $scope.setSearchingTo = newValue => {
        $scope.searching = newValue
      }

      $scope.focusSearch = function () {
        if (!$scope.searching) {
          const mobileNavEle = document.getElementsByClassName('mobile-nav')[0]
          if (mobileNavEle) {
            utilService.trapFocusOn(mobileNavEle)
          }
          $scope.focusSearchBar = true
          $scope.searching = true
          $scope.hideSearch = false
          if ($scope.inListOrCartPage) {
            $scope.showSearchFromClick = true
          }
        } else {
          $scope.hideSearch = $scope.inCheckoutOrList
          $scope.showSearchFromClick = false
          $scope.searching = false
          $scope.focusSearchBar = false
        }
      }

      $scope.isCloseButtonVisible = () => {
        return $scope.inCheckoutConfirmationPage && !utilService.isMobileDotComHeaderPresent()
      }

      $scope.showSearchBar = () => {
        return (
          (!$scope.inCheckoutOrList || $scope.showSearchFromClick) &&
          ($scope.forceDefaultSearchBar || !$scope.customHeaderIsLoading)
        )
      }

      $scope.mobileNavHeaderClick = () => {
        $rootScope.$broadcast(MOBILE_HEADER_CLICKED)
      }

      const handleAngularElementScroll = () => {
        angular
          .element(document.querySelector('[ui-view="content"]'))
          .on('scroll', mobileNavSearchBarFunctionality)
      }

      $rootScope.$on('searchProcessingComplete', () => {
        if (config.autoHideMobileSearchBarOnScroll) {
          handleAngularElementScroll()
        }
      })

      // Set event listeners to show hide/nav search on scroll
      if (config.autoHideMobileSearchBarOnScroll) {
        $document.on('touchmove', mobileNavSearchBarFunctionality)
        $document.on('wheel', mobileNavSearchBarFunctionality)
        handleAngularElementScroll()
        $rootScope.$on('scrollPositionChanged', mobileNavSearchBarFunctionality)
      }
    }
  )
