import angular from 'angular'

export default angular
  .module('modalOptionsServiceModule', [])
  .service('modalOptions', function modalOptions() {
    const service = this
    const DEFAULT_OPTIONS = {
      showCloseButton: true,
      showBackButton: true,
      showTimeslotSelectLaterLink: true,
    }

    service.options = { ...DEFAULT_OPTIONS }

    service.setOptions = newOptions => {
      service.options = {
        ...service.options,
        ...newOptions,
      }

      return service
    }

    service.reset = () => {
      service.options = { ...DEFAULT_OPTIONS }

      return service
    }

    service.isBackButtonVisible = () => {
      return service.options.showBackButton
    }

    service.isCloseButtonVisible = () => {
      return service.options.showCloseButton
    }

    service.isTimeslotSelectLaterLinkVisible = () => {
      return service.options.showTimeslotSelectLaterLink
    }
  })
