import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('OfferIconComponentModule', [])
  .directive('offerIcon', function (config) {
    return {
      scope: {
        offer: '=',
      },
      template: '<div class="offer-icon" ng-class="offerIcon()" style="{{offerImage}}"></div>',
      replace: true,
      controller: function ($scope, $element) {
        // A function to update the styling for offers with associated images. Immediately invoked.
        const updateOfferImage = () => {
          if (
            $scope.offer &&
            $scope.offer.images &&
            $scope.offer.images.tile &&
            $scope.offer.images.tile.medium
          ) {
            $scope.offerImage = 'background-image: url("' + $scope.offer.images.tile.medium + '")'
          } else {
            $scope.offerImage = ''
          }
        }
        updateOfferImage()

        // A watcher to make sure images get updated if the offer call is too slow
        $scope.$watch('offer', (newOffer, oldOffer) => {
          if (_.isEmpty($scope.offerImage) || newOffer !== oldOffer) {
            updateOfferImage()
          }
        })

        $scope.offerIcon = function () {
          if (
            $scope.offer &&
            $scope.offer.reward.type == 'percent' &&
            $scope.offer.reward.value == 100
          ) {
            return 'amount'
          } else {
            // Default to the cart icon if the offer is waiting.
            return $scope.offer
              ? config.offerIcons[$scope.offer.reward.type]
              : config.offerIcons.cart
          }
        }
      },
    }
  })
