import angular from 'angular'
import dayjs from 'dayjs'
import * as reservationSelectors from '@app/services/reservation/selectors'
import * as thunks from '@app/services/reservation/thunks'
import { actions } from '@app/services/reservation/slice'

export default angular
  .module('ReservationServicesModule', [])

  .service(
    'reservationService',
    function reservationService(
      $ngRedux,
      $rootScope,
      Restangular,
      auth,
      config,
      oneCartService,
      timeslotService
    ) {
      const service = this

      const mapStateToService = state => ({
        previewTimeslot: reservationSelectors.defaultReservationSelector(state),
        reservation: reservationSelectors.selectedReservationSelector(state),
        displayReservation: reservationSelectors.currentReservationSelector(state),
      })

      $ngRedux.connect(mapStateToService, {
        ...actions,
        ...thunks,
      })(service)

      // Listening for every time the user context intent or store changes
      // and deleting the reservation when necessary
      // (even if we do not have one stored in this service, it may not be synced with what is stored in
      //  the backend and we still need to signal it to be deleted on the backend until they can do this themselves)
      let currentContext
      let currentUserStoreId
      let currentDeliveryPostalCode

      $rootScope.$on('auth', (event, action, user) => {
        if (action === 'contextUpdated' || action === 'userUpdated') {
          // Check for context, user store change or deliveryPostalCode change.
          const oldContext = currentContext
          const oldUserStoreId = currentUserStoreId
          const oldDeliveryPostalCode = currentDeliveryPostalCode
          currentContext = user && user.context && user.context.intent
          currentUserStoreId = user && user.store && user.store.id
          currentDeliveryPostalCode = user && user.context && user.context.deliveryPostalCode

          // If the context changes to a new non-'in-store list' context, or store changes, delete reservation
          // (we ignore if oldContext, oldUserStoreId or oldDeliveryPostalCode is undefined, means app is loading up)
          if (
            oldDeliveryPostalCode &&
            currentDeliveryPostalCode &&
            currentDeliveryPostalCode !== oldDeliveryPostalCode
          ) {
            service.deleteReservation() // Always delete on a postal code change for safety.
          } else if (
            (oldContext &&
              currentContext &&
              currentContext !== 'instore' &&
              oldContext !== currentContext) ||
            (oldUserStoreId && currentUserStoreId && currentUserStoreId !== oldUserStoreId)
          ) {
            service.fetch()
          }
        }
      })

      service.updateTimeslotPreview = (cartId, notifyOnError) => {
        // Don't update the timeslot preview if a reservation already exists
        if (service.reservation) return

        const currentContext = auth.getContext()
        if (
          currentContext &&
          currentContext.intent &&
          config.shoppingContextProcess[auth.getCurrentShoppingMode()]
            ?.timeslotSelectionAtCheckoutOnly
        ) {
          timeslotService.getPreviewTimeslot(cartId, notifyOnError).then(timeslot => {
            service.setDefaultReservation(timeslot)
          })
        } else {
          service.setDefaultReservation(undefined)
        }
      }

      service.fetch = function (querystring = {}) {
        // reservations not needed for one cart
        if (oneCartService.enabled()) return Promise.resolve(null)

        return service.fetchReservation({
          userIntent: auth.user?.context?.intent,
          querystring,
        })
      }

      service.fetchWithCart = cartId => {
        service.fetch({ cart_id: cartId })
      }

      service.makeReservation = function (reservation) {
        return service.createReservation(reservation)
      }

      // Check to see if there is a reservation and if the reservation time is inside of the lead_time window.

      service.shouldShowLeadTimeAlert = function (product) {
        let formattedReservationFromTime
        let leadTimeEndTime

        if (service.reservation) {
          formattedReservationFromTime = `${service.reservation.fulfillment_date.split('T')[0]}T${
            service.reservation.timeslot.from_time
          }`
          leadTimeEndTime = dayjs().add(product.product_config.lead_time, 'minutes')
        }

        return (
          config.showLeadTimeAlert &&
          service.reservation &&
          dayjs(formattedReservationFromTime).diff(leadTimeEndTime.toLocaleString(), 'seconds') <= 0
        )
      }
    }
  )

  .run(($interval, $rootScope, cartService, config, errorModalService, reservationService) => {
    if (config.enableExpiredTimeslotErrorModal) {
      // Watch for reservations to expire

      $interval(() => {
        if (reservationService.reservation) {
          const seconds = dayjs
            .utc(reservationService.reservation.expiration)
            .diff(dayjs().utc(), 'seconds')
          if (seconds <= 0 && !cartService.cart.order) {
            reservationService.deleteReservation()
            errorModalService
              .open(
                config.lang.expiredTimeslotReservationCompleteError,
                'Ok',
                '',
                config.lang.expiredReservationError
              )
              .finally(() => $rootScope.$broadcast('reservationExpired'))
          }
        }
      }, 30000)
    }
  })
