import angular from 'angular'

export default angular
  .module('icExpressModalServicesModule', [])
  .service('icExpressModalService', function icExpressModalService($uibModal) {
    /**
     * Opens a ic express modal dialog.
     * */
    this.open = initialStep => {
      return $uibModal.open({
        template: `
          <div id="icExpressModal" class="modal-container ic-express-modal-container">
            <!--The modal header is needed so that on mobile the close button is visible-->
            <div class="modal-header ic-express-modal-header"></div>
            <div class="modal-content">
              <button class="close" ng-click="$ctrl.dismiss()" aria-label="{{::config.lang.aria.modalClose}}" auto-focus="true">
                <span class="icon-delete" aria-hidden="true"></span>
              </button>
              <ic-express-entry
                initial-step="$ctrl.initialStep"
                on-success-redirect="$ctrl.dismiss()"
              ></ic-express-entry>
            </div>
          </div>
          `,
        controller($uibModalInstance) {
          const $ctrl = this
          $ctrl.dismiss = $uibModalInstance.dismiss
          $ctrl.initialStep = initialStep
        },
        controllerAs: '$ctrl',
      }).result
    }
  })
