import angular from 'angular'

export default angular
  .module('PhoneInputComponentModule', [])
  .directive('phoneInput', function ($filter) {
    function link(scope) {
      scope.inputValue = scope.phoneInputModel
      scope.$watch('inputValue', function (value) {
        const sanitizedValue = String(value).replace(/[^0-9]+/g, '')
        scope.phoneInputModel = sanitizedValue
        scope.inputValue = $filter('phoneNumber')(sanitizedValue)
      })
    }

    return {
      link,
      restrict: 'E',
      scope: {
        phoneInputLabel: '@label',
        phoneInputId: '@componentId',
        phoneInputErrorId: '@errorId',
        phoneInputModel: '=model',
      },
      template: `
      <input
        id="{{::phoneInputId}}"
        class="input"
        type="tel"
        name="phoneNumber"
        aria-label="{{::phoneInputLabel}}"
        aria-describedby="{{::phoneInputErrorId}}"
        placeholder="{{::phoneInputLabel}}"
        ng-model="inputValue"
        ng-model-options="{allowInvalid: true}"
        required
        update-on-enter
        validate-phone
      />
      <span class="validation-icon" aria-hidden="true"></span>
    `,
    }
  })
  .directive('validatePhone', function () {
    const PHONE_REGEXP = /^\(\d{3}\)\s?\d{3}-\d{4}$/
    return {
      require: 'ngModel',
      link(scope, elem, attrs, ctrl) {
        ctrl.$validators.phone = function (modelValue) {
          return modelValue === '' || PHONE_REGEXP.test(modelValue)
        }
      },
    }
  })
  .filter('phoneNumber', function () {
    // Format phone number specific to North American use case
    return function (phoneNumber = '') {
      let formattedPhoneNumber = ''

      if (phoneNumber) {
        phoneNumber
          .slice(0, 10)
          .split('')
          .forEach((digit, index) => {
            if (index === 0) {
              formattedPhoneNumber += '('
            } else if (index === 3) {
              formattedPhoneNumber += ') '
            } else if (index === 6) {
              formattedPhoneNumber += '-'
            }

            formattedPhoneNumber += digit
          })
      }

      return formattedPhoneNumber
    }
  })
