import angular from 'angular'

export default angular
  .module('PasswordChangeComponentModule', [])

  .config(($ocLazyLoadProvider, $stateProvider, langProvider) => {
    $stateProvider.state('main.account.passwordChange', {
      url: '/password',
      views: {
        'content@main.account': {
          template: require('./passwordChange.html'),
          controller: 'PasswordChangeController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "passwordChangeController", webpackPrefetch: true */ './passwordChangeController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.changePassword)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.changePassword,
          },
        ],
      },
    })
  })
