/** **************************
 * CHECKOUT ROUTE CONSTANTS *
 *************************** */

export const CHECKOUT_VERSIONS = {
  V1: 'V1',
  V2: 'V2',
}

export const CHECKOUT_ROUTE_PREFIXES = {
  [CHECKOUT_VERSIONS.V1]: '',
  [CHECKOUT_VERSIONS.V2]: '/v2',
}

export const CHECKOUT_ROUTES_FACTORY = newCheckoutConfig => {
  const { V1, V2 } = CHECKOUT_ROUTE_PREFIXES

  return {
    [CHECKOUT_VERSIONS.V1]: {
      CART: `${V1}/cart`,
      UPSELL: `${V1}/get-more`,
      // CHECKOUT_REVIEW - there isn't a single route for v1
    },
    [CHECKOUT_VERSIONS.V2]: {
      CART: `${V2}/cart`,
      UPSELL: `${V2}/${newCheckoutConfig.upsellPage.routeName}`,
      CHECKOUT_REVIEW: `${V2}/review`,
    },
  }
}
