import angular from 'angular'

export default angular.module('ElementMatchPollyfillComponentModule', []).config(() => {
  // patches Element#matches
  window.Element &&
    (function (ElementPrototype) {
      ElementPrototype.matches =
        ElementPrototype.matches ||
        ElementPrototype.matchesSelector ||
        ElementPrototype.mozMatchesSelector ||
        ElementPrototype.msMatchesSelector ||
        ElementPrototype.oMatchesSelector ||
        ElementPrototype.webkitMatchesSelector ||
        function (selector) {
          const node = this
          const nodes = (node.parentNode || node.document).querySelectorAll(selector)
          let i = -1
          // eslint-disable-next-line no-plusplus, eqeqeq
          while (nodes[++i] && nodes[i] != node) {}
          return !!nodes[i]
        }
    })(Element.prototype)
})
