import _ from 'lodash'
import angular from 'angular'
import { getSortOptions } from '@app/utils/cart'
import {
  areAllItemsRx,
  getSalePrice,
  inCateringMode,
} from '@instacart/enterprise-services/shopping-list/_common/utils'

const SIDE_CART = 'Side Cart'

export default angular
  .module('SideCartControllerModule', [])
  .controller(
    'SideCartController',
    function (
      $controller,
      $element,
      $rootScope,
      $scope,
      $state,
      $timeout,
      auth,
      cartAnalytics,
      cartService,
      config,
      ebtCardService,
      lang,
      listPrintService,
      mobileService,
      oneCartService,
      rateApp,
      shoppingSelectorService,
      sideCartSortModalService,
      undoRemove,
      utilService
    ) {
      $controller('CheckoutCartController', {
        $scope,
        // upSellProducts is a dependency for the CheckoutCartController because it is needed in the main.checkout.cart state.
        // To satisfy the dependency it is passed in as an empty list here.
        upsellProducts: [],
      })

      const SORT_OPTIONS = getSortOptions(lang)

      $scope._ = _
      $scope.config = config
      $scope.mobile = mobileService
      $scope.newCustomItem = {
        name: '',
      }
      $scope.sideCartMenu = {
        isOpen: false,
      }
      $scope.sortType = 'category'
      $scope.showSortModal = false
      $scope.listPrintService = listPrintService

      $scope.setSortType = function (type) {
        $scope.sortType = type
      }
      $scope.shoppingSelectorService = shoppingSelectorService

      // As the user scrolls we adjust the height of the side cart, this ensures layout is correct
      // We only need to do this to support flex in the non-one-cart desktop view
      if (!mobileService.isMobile && !oneCartService.enabled()) {
        $scope.sideCartPositionChange = el => {
          const elTop = el[0].getBoundingClientRect().top
          el.css('height', `calc(100% - ${elTop}px`)
        }
        $timeout(() => $scope.sideCartPositionChange($element), 100) // Initial height sync
      }

      $scope.onContinueShopping = () => {
        if ($scope.mobile.isMobile) {
          $scope.closeSideCart()
        } else {
          $scope.handleContinueShoppingClick()
        }
      }

      $scope.closeSideCart = function () {
        $rootScope.$broadcast('sideCart', 'close')
      }

      $scope.handleSideCartReView = function () {
        const el = document.getElementById('side-cart-container')
        utilService.getFocusableElements(el)[0].focus()
      }

      $scope.handleSideCartSkip = function () {
        const el = document.getElementById('content')
        utilService.getFocusableElements(el)[0].focus()
        $scope.closeSideCart()
      }

      $scope.handleContinueShoppingClick = () => {
        if (oneCartService.enabled()) {
          $scope.closeSideCart()
        }
      }

      $scope.auth = auth
      Object.defineProperty($scope, 'inStoreIntent', {
        get: () => auth.getContext().intent === 'instore',
      })
      Object.defineProperty($scope, 'sideCartLabel', {
        get: () => {
          return (
            lang.values.sideCartTitleLabel ||
            ($scope.inStoreIntent
              ? config.shoppingSelector.instore.shortTitle || config.shoppingSelector.instore.title
              : lang.values.cart)
          )
        },
      })

      $scope.isShowCartMinimumAlert = dataByCartType => {
        const userHasEbtCardAndEbtItems =
          config.ebt.enabled && ebtCardService.userHasEbtCard && cartService.cartHasEbtItem()

        const isCartBelowMin = config.useMultiContextCart
          ? // Check if there is a cart that has not reached a cart min yet (all carts need to be above the min)
            Object.keys(dataByCartType)?.length === 0 ||
            Object.values(dataByCartType).some(
              cart => cart.cartMinMsg && !areAllItemsRx(cart.items)
            )
          : cartService.isCartBelowMin()

        return (
          isCartBelowMin && !userHasEbtCardAndEbtItems && !areAllItemsRx(cartService.cart?.items)
        )
      }

      $scope.showOrderOnlineButton = cartService.canOrderOnline()

      // check for app review
      const cartLength = _.get(cartService, 'cart.items.length', 0)
      const listItemsToReview = config.mobileAppRate.listItemsReviewed
      // eslint-disable-next-line eqeqeq
      if (listItemsToReview != -1) {
        if (cartLength >= listItemsToReview && $scope.inStoreIntent) {
          rateApp.promptAppReview()
        }
      }

      $scope.checkItem = item => {
        cartService.currentEnterpriseService.actions.updateItemChecked(item, item.checked)
        cartService.sync()
      }

      // Handle custom items

      $scope.addCustomItem = function (itemName) {
        if (itemName.length) {
          cartService.addItem(itemName, 1)
          $scope.newCustomItem.name = ''
        }
      }

      $scope.removeCustomItem = function (item) {
        cartService.removeItem(item.pseudo_product.name)
      }

      $scope.toggleAddCustom = function (value) {
        $scope.showAddCustom = value
        if ($scope.showAddCustom) {
          const customItemInput = document.querySelector('.add-custom-item > form > input')
          customItemInput.focus()
        } else {
          utilService.blurAndHideKeyboard()
        }
      }

      $scope.customItems = function () {
        if (cartService.cart) {
          const customItems = _.filter(cartService.cart.items, {
            item_type: 'pseudo_product',
          })
          return customItems
        }
      }

      $scope.shouldShowSidecart = () => {
        // We do not wish to show the side cart in any of the following "exception" routes
        if (mobileService.isMobile && !oneCartService.enabled()) return false

        const exceptionRoutes = [
          'main.sideCart',
          'main.checkout',
          'main.inStoreList.review',
          'main.subscriptions',
        ]
        const inExceptionRoute = _.some(exceptionRoutes, exception => $state.includes(exception))
        const cartHasItems = cartService.cart && cartService.cart.items.length > 0

        const shouldShowSidecart =
          !inExceptionRoute && (cartHasItems || $scope.isOneCartEnabled() || inCateringMode())

        $rootScope.showingSideCart = shouldShowSidecart
        return shouldShowSidecart
      }

      $scope.isOneCartEnabled = () => oneCartService.enabled()

      $scope.isItemEbtEligible = item =>
        item.store_product.usa_snap_eligible &&
        ebtCardService.userHasEbtCard &&
        auth.user.store?.ebt_enabled_in_state

      $scope.overrideSalePrice = item => {
        if (
          config.useSalePricingForOffersInSideCartCostcoOnly &&
          item.quantity !== 0 &&
          cartService.itemHasOffer(item)
        ) {
          return getSalePrice(item)
        }
      }

      $scope.$on('$destroy', () => {
        $rootScope.showingSideCart = false
      })

      $scope.sortOptionNameByValue = value =>
        SORT_OPTIONS.find(option => option.value === value)?.name

      $scope.triggerSortModal = () => {
        $scope.showSortModal = true
        sideCartSortModalService
          .open({
            sortOptions: SORT_OPTIONS,
            sort: $scope.sortType,
          })
          .then(
            results => {
              $scope.setSortType(results.sort)
              $scope.showSortModal = false
            },
            () => {
              $scope.showSortModal = false
            }
          )
      }

      /**
       * Adds a given *quantity* of items to the cart
       *
       * @param {object} item
       * @param {number} quantity
       * @param {number} index - position of the item in the side cart
       *
       * @returns {undefined}
       */
      $scope.addItem = (item, quantity, index) => {
        cartAnalytics.addItem(item.store_product, quantity, index, SIDE_CART, {})
        cartService.incrementItem(item.store_product, quantity, item.product_config)
      }

      /**
       * Focuses on the item remove confirmation dialog of a given item
       * @param {object} item - selected item
       * @param {number} index - selected item index
       *
       * @returns {undefined}
       */
      $scope.focusOnDeleteConfirmation = (item, index) => {
        const el = document.querySelector(`#item-remove-alert-${item.id}-${index}`)

        el.focus()
        utilService.trapFocusOn(el)
      }

      /**
       * Removes a given *quantity* of items from the cart
       *
       * @param {object} item
       * @param {number} quantity
       * @param {number} index - position of the item in the side cart
       * @param {boolean} wasConfirmed
       * @returns {undefined}
       */
      $scope.removeItem = (item, quantity, index, wasConfirmed) => {
        cartAnalytics.removeItem(item.store_product, quantity, index, SIDE_CART, {})

        const { action, result } = cartService.decrementItem(
          item.store_product,
          quantity,
          item.product_config
        )
        if (
          !wasConfirmed &&
          action === cartService.updateActions.remove &&
          mobileService.isMobile &&
          result?.then
        ) {
          result.then(() => undoRemove.showToast(item))
        }
      }

      /**
       * Triggered when the weight is updated
       *
       * @param {object} item                - The product item being updated
       * @param {number} $originalQuantity   - [Provided] The original quantity before the change
       * @param {function} $onZeroFn         - [Provided] Callback to trigger if the new quantity is zero
       *
       * @returns {undefined}
       */
      $scope.updateQuantity = (item, $originalQuantity, $onZeroFn) => {
        const isZero = item.quantity === 0
        if ($onZeroFn && isZero) {
          $onZeroFn()
          item.quantity = $originalQuantity
          return
        }

        const { store_product, quantity, product_config } = item
        cartService.updateItemQuantity(store_product, quantity, product_config?.options)
      }

      $scope.hasEligibleEbtCard = () =>
        ebtCardService.userHasEbtCard && auth.user.store?.ebt_enabled_in_state
    }
  )
