import angular from 'angular'

export default angular.module('BannerSelectorComponentModule', []).component('bannerSelector', {
  bindings: {
    banners: '<',
    hideUnavailableBanners: '<',
    availableBannerSlugs: '<',
    selected: '<',
    onSelect: '&',
    onShopAnotherWay: '&',
  },
  controller(lang) {
    const getFilterdBanners = () => {
      if (this.hideUnavailableBanners) {
        return this.banners.filter(banner => this.availableBannerSlugs.includes(banner.bannerSlug))
      }
      return this.banners
    }

    Object.assign(this, {
      filteredBanners: getFilterdBanners(),
      lang,
      isBannerAvailable: bannerSlug => this.availableBannerSlugs?.includes(bannerSlug),
      selectBanner: bannerSlug => this.onSelect({ bannerSlug }),
    })
  },
  template: `
      <div
        class="banner-container"
        ng-class="{
          selected: banner.bannerSlug === $ctrl.selected
        }"
        ng-repeat="banner in $ctrl.filteredBanners track by banner.bannerSlug"
      >
        <div class="banner-image-container">
          <banner-image banner-slug="banner.bannerSlug"></banner-image>
        </div>
        <div class="banner-name" ng-class="{'has-error': !$ctrl.isBannerAvailable(banner.bannerSlug)}">
          <span>{{banner.name}}</span>
          <p class="selection-error-msg" ng-if="!$ctrl.isBannerAvailable(banner.bannerSlug)">
            {{::$ctrl.lang.values.deliveryUnavailableAtZip}}
          </p>
        </div>
        <div>
          <button
            class="button hollow small"
            type="button"
            ng-if="$ctrl.isBannerAvailable(banner.bannerSlug)"
            ng-click="$ctrl.selectBanner(banner.bannerSlug)"
          >
            {{::$ctrl.lang.values.select}}
          </button>
          <button
            class="button hollow small"
            type="button"
            ng-if="!$ctrl.isBannerAvailable(banner.bannerSlug) && !$ctrl.onShopAnotherWay"
            disabled="true"
          >
            {{::$ctrl.lang.values.shopAnotherWay}}
          </button>
          <button
            class="button hollow small"
            type="button"
            ng-if="!$ctrl.isBannerAvailable(banner.bannerSlug) && $ctrl.onShopAnotherWay"
            ng-click="$ctrl.onShopAnotherWay()"
          >
            {{::$ctrl.lang.values.shopAnotherWay}}
          </button>
        </div>
      </div>
    `,
})
