/* eslint-disable global-require */
import { ShoppingListItem } from '@app/types/shoppingList'
import { StoreProduct } from '@app/types/product'
import { Category } from '@app/types/category'
import { Recipe } from '@app/types/recipe'
import escapeImageUrl from '@app/utils/images/escapeImageUrl'
import { isRxProduct } from '@instacart/enterprise-services/product/utils'

export type ImageShape = 'square' | 'rectangle'
export type Size = 'small' | 'medium' | 'large'

const isShoppingListItem = (
  product: ShoppingListItem | StoreProduct
): product is ShoppingListItem => (product as ShoppingListItem).item_type !== undefined

const getProductImageURL = (product: ShoppingListItem | StoreProduct, size?: Size) => {
  // due to possible incoming product as shoppingList item, need to extract storeProduct from it
  let storeProduct: StoreProduct

  // Display rx default image when product is Rx otherwise show store product image
  if (isShoppingListItem(product)) {
    if (isRxProduct(product)) {
      return require('static/images/rx-default-image.png')
    }
    storeProduct = product.store_product
  } else {
    storeProduct = product
  }

  if (storeProduct && size) {
    const imageTile = storeProduct?.images?.tile?.[size]
    if (imageTile) {
      return escapeImageUrl(imageTile)
    }
  }

  return require('static/images/no-product-image.jpg')
}

const getDefaultRecipeImageURL = (shape: ImageShape) => {
  if (shape === 'square') {
    return require('static/images/no-recipe-image-square.png')
  }

  if (shape === 'rectangle') {
    return require('static/images/no-recipe-image-rectangle.png')
  }
  return null
}

const getRecipeImageURL = (recipe: Recipe, shape: ImageShape) => {
  const recipeImageURL = recipe?.media?.[0]?.image?.header

  return recipeImageURL ? escapeImageUrl(recipeImageURL) : getDefaultRecipeImageURL(shape)
}

const getRecipeCategoryImageURL = (recipeCategory: Category, size?: Size) => {
  const defaultSize = size || 'medium'
  const recipeCategoryImageURL = recipeCategory.images?.tile?.[defaultSize]

  return recipeCategoryImageURL
    ? escapeImageUrl(recipeCategoryImageURL)
    : getDefaultRecipeImageURL('square')
}

export { getProductImageURL, getRecipeImageURL, getRecipeCategoryImageURL }
