import _ from 'lodash'
import angular from 'angular'
import template from './facetedSideFilters.html'

export default angular.module('FacetedSideFiltersModule', []).component('facetedSideFilters', {
  template,
  bindings: {
    facetedSideFilters: '<',
  },
  controller($scope, $state, $stateParams, $timeout, facetedSearchService, lang, utilService) {
    const vm = this

    // a11y feature to force select of next sequential facet option on group expansion with "Show All" button
    const getFacetFocusOnGroupExpand = (facetGroupIndex, facetIndex) =>
      document
        .querySelectorAll('faceted-side-filters fieldset')
        [facetGroupIndex].querySelectorAll('faceted-side-filters-checkbox')[facetIndex]

    vm.baseNumberShown = 4
    vm.lang = lang
    vm.facetShowMore = _.map(
      facetedSearchService.categorizedFacetsObject.sideFilters,
      () => vm.baseNumberShown
    )

    $scope.selectedFacetedSideFilters = {}
    $scope.selectedFacetedSideBrands = {}

    if ($stateParams.brand_name || $stateParams.brand_names) {
      const brandNamesParamArray = $stateParams.brand_name || $stateParams.brand_names

      if (_.isArray(brandNamesParamArray)) {
        _.each(brandNamesParamArray, brandName => {
          $scope.selectedFacetedSideBrands[brandName] = true
          facetedSearchService.setTagCrumbs(true, brandName, 'brand_name')
        })
      } else {
        $scope.selectedFacetedSideBrands[brandNamesParamArray] = true
        facetedSearchService.setTagCrumbs(true, brandNamesParamArray, 'brand_name')
      }
    }

    if ($stateParams.tags) {
      if (_.isArray($stateParams.tags)) {
        _.each($stateParams.tags, tag => {
          $scope.selectedFacetedSideFilters[tag] = true
          facetedSearchService.setTagCrumbs(true, tag, 'tags')
        })
      } else {
        $scope.selectedFacetedSideFilters[$stateParams.tags] = true
        facetedSearchService.setTagCrumbs(true, $stateParams.tags, 'tags')
      }
    }

    vm.showOrHideMore = (facetGroupIndex, totalNumber) => {
      const indexToFocus = totalNumber ? vm.baseNumberShown : vm.baseNumberShown - 1
      vm.facetShowMore[facetGroupIndex] = totalNumber || vm.baseNumberShown

      // a11y feature to select sequential option after showing more options, must use $timeout because of angular digest cycle
      $timeout(() =>
        utilService.focusWithin(getFacetFocusOnGroupExpand(facetGroupIndex, indexToFocus))
      )
    }

    // Wait for `facetedSearchService.getAllFacets` to successfully return data in `searchController`
    $scope.$on('facetsDataReceived', () => {
      vm.facetShowMore = _.map(
        facetedSearchService.categorizedFacetsObject.sideFilters,
        () => vm.baseNumberShown
      )
    })
  },
})
