import _ from 'lodash'
import angular from 'angular'
import { AUTH_ACTIONS } from '@app/angular/constants/authActions'

export default angular
  .module('AccountNavMenuServiceModule', [])
  .service(
    'accountMenuService',
    function accountMenuService(
      $injector,
      auth,
      config,
      eComLightService,
      lang,
      mobileService,
      oneCartService,
      policyService,
      userService,
      utilService
    ) {
      const service = this

      // eslint-disable-next-line complexity
      service.getAccountMenuItems = () => {
        let menu = [
          {
            sref: 'main.account.profile',
            label: lang.values.profile,
          },
        ]

        // Added externalProfileURL to for CRM integrations where Unata will not allow profile details updates.
        // This will take the customers to the external page where the updates can be made.
        if (config.externalProfileURL) {
          menu = [
            {
              extLink: config.externalProfileURL,
              label: lang.values.profile,
            },
          ]
        }

        // Add external password change link for clients where we don't support password change in our application.
        // Makes a check for special cases if the retailer is using Gigya IDP. If enabled, use Gigya Screen-set
        // If disabled, use `main.account.passwordChange`

        if (!config.disableChangePassword) {
          if (config.gigya && config.gigya.enabled) {
            menu = menu.concat([
              {
                extLink: 'gigya',
                label: lang.values.passwordChange,
              },
            ])
          } else if (config.externalPasswordChangeURL) {
            menu = menu.concat([
              {
                extLink: config.externalPasswordChangeURL,
                label: lang.values.passwordChange,
              },
            ])
          } else if (config.auth?.serviceName) {
            // Use the change password flow of the custom auth service
            const authService = auth.getAuthService()
            menu = menu.concat([
              {
                onClick: () => authService.init(AUTH_ACTIONS.CHANGE_PASSWORD),
                label: lang.values.passwordChange,
              },
            ])
          } else {
            menu = menu.concat([
              {
                sref: 'main.account.passwordChange',
                label: lang.values.passwordChange,
              },
            ])
          }
        }

        // Don't insert address book link if disabled
        if (
          !config.accountMenuOptions.disableAddressBook &&
          config.enableMultipleDeliveryAddresses
        ) {
          menu.push({
            sref: 'main.account.addressBook',
            label: lang.values.addressBookTitle,
          })
        }

        // Don't insert notification preferences link if disabled
        if (!config.disableNotificationPreferences) {
          menu = menu.concat([
            {
              sref: 'main.account.notifications',
              label: lang.values.notificationPreferences,
            },
          ])
        }

        // TODO: These should be made configurable

        if (!config.accountMenuOptions.disableOrderHistory) {
          menu = menu.concat([
            {
              sref: 'main.account.orderHistory',
              label: lang.values.orderHistory,
            },
          ])
        }

        if (config.icExpress.enabled) {
          menu.push({
            sref: 'main.account.icExpress',
            label: config.icExpress.name,
            ngIf: () => auth.user.ic_linked,
          })
        }

        if (!config.accountMenuOptions.disableSubstitutions && !eComLightService.inEComLightMode) {
          menu = menu.concat([
            {
              sref: 'main.account.substitutions',
              label: lang.values.substitutionPreferences,
            },
          ])
        }

        if (
          !(config.accountMenuOptions.disableSaveForLaterLists || config.disableSaveForLaterLists)
        ) {
          menu = menu.concat([
            {
              sref: 'lists',
              label: lang.values.saveForLaterLists,
            },
          ])
        }

        // Insert saved recipes link

        if (config.enableSavedRecipes && !config.disableRecipes) {
          menu = menu.concat([
            {
              sref: 'main.shop.recipes.saved',
              label: lang.values.savedRecipes.savedRecipes,
            },
          ])
        }

        if (!config.accountMenuOptions.disableAvailableCredits) {
          menu = menu.concat([
            {
              sref: 'main.account.credits',
              label: lang.values.availableCredits,
            },
          ])
        }

        // Insert if in price alerts are not disabled

        if (
          !config.disablePriceAlerts &&
          !(config.disabledMobileAccountMenu.priceAlerts && mobileService.isMobile)
        ) {
          menu = menu.concat([
            {
              sref: 'main.account.priceAlerts',
              label: lang.values.priceAlerts,
            },
          ])
        }

        if (
          oneCartService.enabled() &&
          config.ebt?.enabled &&
          auth.user.store?.ebt_enabled_in_state
        ) {
          menu = menu.concat([
            {
              sref: 'main.account.snapEBT',
              label: lang.values.ebt.EBTSNAP,
            },
          ])
        }

        // Insert loyalty item if needed

        // console.log('Config - has loyalty: ', config.hasLoyalty)
        // console.log('Config - !disable loyalty links: ', !config.disableLoyaltyLinks)
        // console.log('1: ', (config.customerLoyaltyOverride && userService.loyaltyCards.length))

        if (
          config.hasLoyalty &&
          !config.disableLoyaltyLinks &&
          ((config.customerLoyaltyOverride && userService.loyaltyCards.length) || // 1
            ((!config.disableLoyaltyWhenAccountless || userService.loyaltyCards.length) &&
              (!config.disableLoyaltyWhenEmployeeIdless ||
                (auth.user.ext_data && auth.user.ext_data.employee_id)))) &&
          !(config.disabledMobileAccountMenu.loyaltyCard && mobileService.isMobile)
        ) {
          menu.splice(1, 0, {
            sref: 'main.account.loyalty',
            label: lang.values.loyaltyCard,
          })
        }

        // Insert subscription item if needed

        if (
          config.hasSubscriptions &&
          !(config.disabledMobileAccountMenu.subscription && mobileService.isMobile)
        ) {
          menu.splice(6, 0, {
            sref: 'main.account.subscription',
            label: lang.values.subscription,
          })
        }

        // Insert rate products item if needed

        if (
          !config.disableRateProducts &&
          !(config.disabledMobileAccountMenu.rateProducts && mobileService.isMobile)
        ) {
          menu.push({
            sref: 'main.account.rateProducts',
            label: lang.values.rateProducts,
          })
        }

        // Insert pricing policy if needed
        if (
          config.policyModal.showPricingPolicyInHeader &&
          config.policyModal.isPricingPolicyEnabled
        ) {
          menu.push({
            onClick: () => policyService.open('pricing'),
            label: lang.values.viewPricingPolicy,
          })
        }

        // Insert any custom links

        if (config.accountMenu && config.accountMenu.length) {
          // eslint-disable-next-line no-plusplus
          for (let ii = 0; ii < config.accountMenu.length; ii++) {
            menu.splice(
              config.accountMenu[ii].position || config.accountMenu[ii].index,
              0,
              config.accountMenu[ii]
            )
          }
        }

        // Add `id` tag labels to account menu links that have an `sref`
        menu = _.map(menu, utilService.generateAddIdToSrefLinkWithPrefix('account-menu'))

        return menu
      }

      return service
    }
  )
