import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { SHOPPING_SELECTOR_STAGES } from '@app/angular/constants/shoppingSelectorStages'

export default angular
  .module('ParentShoppingSelectorControllerComponentModule', [])
  .controller(
    'ParentShoppingSelectorController',
    function (
      $rootScope,
      $scope,
      $state,
      $timeout,
      addressList,
      auth,
      config,
      initialSelectorErrorMessage,
      lang,
      mobileService,
      modalOptions,
      shoppingContextService,
      shoppingSelectorService,
      shoppingSelectorTarget
    ) {
      $scope.auth = auth
      $scope.addressList = addressList
      $scope.config = config
      $scope.lang = lang
      $scope.shoppingSelectorStage = shoppingSelectorTarget
      $scope.mobile = mobileService
      $scope.modalOptions = modalOptions
      $scope.shopContextIntent =
        shoppingContextService.selectedFulfillmentType ||
        auth.getContext().intent ||
        config.defaultShoppingContext

      $scope.shoppingContextText = shoppingContextService.getShoppingModeTitle(
        $scope.shopContextIntent
      )
      $scope.hasPreviousShoppingSelectorStage = false
      $scope.previousStoreSelectorShoppingState = SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT

      $scope.$watch('shoppingSelectorStage', (newStage, oldStage) => {
        // Only clear when moving from one stage to another stage, not when initializing the first stage.
        if (newStage !== oldStage) {
          $scope.clearShoppingSelectorStageErrorMessage(oldStage)
          $scope.hasPreviousShoppingSelectorStage = true
          $scope.previousStoreSelectorShoppingState = oldStage
        }
        $scope.currentShoppingStage = newStage
        if (shoppingContextService.selectedFulfillmentType) {
          $scope.shopContextIntent = shoppingContextService.selectedFulfillmentType
        }
        if (
          $scope.shopContextIntent &&
          config.shoppingSelector[$scope.shopContextIntent].fulfilledByInstacart &&
          config.shoppingSelector[$scope.shopContextIntent].poweredByTitle
        ) {
          $scope.shoppingContextText =
            config.shoppingSelector[$scope.shopContextIntent].poweredByTitle
        } else if ($scope.shopContextIntent) {
          $scope.shoppingContextText =
            config.shoppingSelector[$scope.shopContextIntent].title ||
            shoppingContextService.getShoppingModeTitle($scope.shopContextIntent)
        }
        $scope.triggerInputShake = false
      })
      $scope.modalOpen = true

      // Take over escape key behaviour so that it is forced to go through processModalCloseClick
      $scope.$on('modal.closing', (event, reason) => {
        if (reason === 'escape key press') {
          event.preventDefault()
          $scope.processModalCloseClick()
        }
      })

      // Error message value for the Shopping Selector

      $scope.shoppingSelectorErrorMessageList = {
        shoppingContext: null,
        bannerSelector: null,
        cartToListMigration: null,
        storeSelector: null,
        timeslot: null,
        confirmation: null,
      }

      $scope.isThereAnError = function () {
        return $scope.shoppingSelectorErrorMessageList[$scope.shoppingSelectorStage] !== null
      }

      $scope.getErrorMessage = function () {
        return $scope.shoppingSelectorErrorMessageList[$scope.shoppingSelectorStage]
      }

      $scope.invalidFocusFn = {}

      $scope.setShoppingSelectorErrorMessage = function (stage, message) {
        $scope.triggerInputShake = true
        $scope.shoppingSelectorErrorMessageList[stage] = message

        $timeout(() => {
          document.getElementById('shopping-selector-postal-code')?.focus()
        })

        // $timeout is to reset the 0.8s error shake animation
        $timeout(() => {
          $scope.triggerInputShake = false
        }, 800)
      }

      $scope.clearShoppingSelectorStageErrorMessage = function (stage) {
        $scope.shoppingSelectorErrorMessageList[stage] = null
      }

      // This is a bit of a hack - but it is better than the $parent hack that is not reliable
      // Unfortunately the timeslot and shopping stage system is just a mess. It's too arduous to try fix it.
      // eslint-disable-next-line no-multi-assign
      $rootScope.setShoppingSelectorStage = $scope.setShoppingSelectorStage = function (stage) {
        $scope.shoppingSelectorStage = stage
      }

      $scope.reloadContextValues = function () {
        $rootScope.reloadState()
      }

      const hasDeliveryOrPickup =
        config.shoppingSelector.delivery.exists || config.shoppingSelector.pickup.exists

      const numOfEnabledContexts = Object.values(SHOPPING_MODES).reduce((count, shoppingMode) => {
        if (config.shoppingSelector[shoppingMode]?.exists) {
          count += 1
        }
        return count
      }, 0)

      const onlyInstoreEnabled = !hasDeliveryOrPickup

      const hasSavedAddresses = $scope.addressList.hasAddressItems()

      $scope.showBackButtonInStoreSelector = () =>
        $scope.shoppingSelectorStage === SHOPPING_SELECTOR_STAGES.STORE_SELECTOR &&
        hasDeliveryOrPickup &&
        (numOfEnabledContexts > 1 || $scope.hasPreviousShoppingSelectorStage)

      // Only show Close button if...
      $scope.showCloseButton = () => {
        if (!modalOptions.isCloseButtonVisible()) {
          return false
        }

        // If enableMandatoryStoreSelection is enabled and it is not in DEVLIERY,
        // force store selection by preventing the close / automatically set to default store flow
        // if user in delivery context and does not have a valid deliver postal code, do not show close
        const storeSelectorCloseHidden =
          (auth.user.context.intent &&
            auth.user.context.intent !== SHOPPING_MODES.DELIVERY &&
            config.enableMandatoryStoreSelection) ||
          !shoppingSelectorService.isContextIntentInValidState()

        // on Desktop
        if (!mobileService.isMobile) {
          // Note: not sure what is the difference between the `mobile` and `desktop` behaviour, looks the same. Will not remove the similar logic for now
          if (
            $scope.shoppingSelectorStage === SHOPPING_SELECTOR_STAGES.STORE_SELECTOR &&
            storeSelectorCloseHidden
          ) {
            return false
          }
          return true
        }

        // on Mobile and...
        switch ($scope.shoppingSelectorStage) {
          case SHOPPING_SELECTOR_STAGES.CONFIRMATION:
          case SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT:
          case SHOPPING_SELECTOR_STAGES.TIMESLOT:
            return true
          case SHOPPING_SELECTOR_STAGES.STORE_SELECTOR:
            if (storeSelectorCloseHidden) {
              return false
            }
            if (hasSavedAddresses) {
              return true
            }
            // on Store Selector screen and delivery/pickup are not enabled for that retailer
            return onlyInstoreEnabled

          default:
            return false
        }
      }

      $scope.processModalCloseClick = function () {
        $scope.triggerInputShake = false

        if (
          auth.user.context &&
          auth.user.context.intent &&
          auth.user.context.intent === SHOPPING_MODES.DELIVERY &&
          !auth.user.context.deliveryPostalCode
        ) {
          $scope.setShoppingSelectorStage(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
          const errorTemplate = config.shoppingContextProcess.delivery.requireFullAddress
            ? lang.values.deliveryFullAddressRequired
            : lang.values.deliveryPostalCodeRequired
          $scope.setShoppingSelectorErrorMessage(
            SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
            lang.template(errorTemplate, { postalCode: lang.values.postalCode })
          )
        } else if (
          auth.user.context &&
          auth.user.context.intent &&
          auth.user.context.intent === SHOPPING_MODES.PICKUP &&
          !auth.userStoreSupportsPickup()
        ) {
          // This is a workaround fix for the ability to enter an eCom in a store which does not support eCom
          // TODO: Don't change contexts until a user has both selected a context & a store appropriate to that context.
          $scope.setShoppingSelectorStage(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
          $scope.setShoppingSelectorErrorMessage(
            SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
            lang.values.pickupStoreRequired
          )
        } else {
          $scope.clearShoppingSelectorStageErrorMessage($scope.shoppingSelectorStage)
          $scope.modalOpen = false

          // $timeout is to allow the 0.3s zoom out animation to happen
          $timeout(() => $scope.$close(), 300)
        }
      }

      $scope.handleBackButtonClickFrom = origin => {
        switch (origin) {
          case SHOPPING_SELECTOR_STAGES.TIMESLOT:
            $scope.setShoppingSelectorStage(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
            break
          case SHOPPING_SELECTOR_STAGES.STORE_SELECTOR:
            $scope.setShoppingSelectorStage($scope.previousStoreSelectorShoppingState)
            break
          case SHOPPING_SELECTOR_STAGES.BANNER_SELECTOR:
            $scope.setShoppingSelectorStage(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
            break
          default:
            console.error(`The shopping selector "${origin}" doesn't have a back handler.`)
        }
      }

      /**
       * Returns if the back button is visible
       * @returns {boolean}
       */
      $scope.isBackButtonVisible = () => {
        return (
          ($scope.shoppingSelectorStage === SHOPPING_SELECTOR_STAGES.STORE_SELECTOR &&
            $scope.showBackButtonInStoreSelector()) ||
          $scope.shoppingSelectorStage === SHOPPING_SELECTOR_STAGES.TIMESLOT ||
          $scope.shoppingSelectorStage === SHOPPING_SELECTOR_STAGES.BANNER_SELECTOR
        )
      }

      /**
       * Returns the back button ID
       * @returns {string}
       */
      $scope.getBackButtonId = () => {
        switch ($scope.shoppingSelectorStage) {
          case SHOPPING_SELECTOR_STAGES.STORE_SELECTOR:
            return 'shopping-selector-parent-set-shopping-selector-stage-context'
          case SHOPPING_SELECTOR_STAGES.TIMESLOT:
            return 'shopping-selector-set-shopping-selector-stage-store'
          case SHOPPING_SELECTOR_STAGES.BANNER_SELECTOR:
            return 'shopping-selector-set-shopping-selector-stage-banner'
          default:
            return ''
        }
      }

      // Initialize initial selector error message.
      if (shoppingSelectorTarget && initialSelectorErrorMessage) {
        $scope.setShoppingSelectorErrorMessage(shoppingSelectorTarget, initialSelectorErrorMessage)
      }
    }
  )
  .run($templateCache => {
    $templateCache.put('contextSelector.html', require('./contextSelector/contextSelector.html'))
    $templateCache.put('storeSelector.html', require('./storeSelector/storeSelector.html'))
    $templateCache.put('timeslotSelector.html', require('./timeslotSelector/timeslotSelector.html'))
    $templateCache.put(
      'shoppingSelectorConfirmation.html',
      require('./confirmation/shoppingSelectorConfirmation.html')
    )
  })
