import React, { memo } from 'react'
import PropTypes from 'prop-types'
import { Button, Checkbox, createStyles, utils } from '@instacart/cocktail'
import { CARD_PROPS } from '../common/props'

const useStyles = createStyles(tokens => ({
  container: {
    display: 'block',
    width: '100%',
    height: '100%',
  },
  title: {
    marginBottom: tokens.size.spacing.xSmall,
    fontSize: tokens.size.font.large,
    fontWeight: tokens.font.weight.bold,
  },
  card: {
    position: 'relative',
    whiteSpace: 'nowrap',
    padding: utils.toPx`${tokens.size.spacing.xSmall} ${tokens.size.spacing.medium}`,
  },
  cardSelected: {
    backgroundColor: tokens.color.neutral.grey1,
    padding: utils.toPx`${tokens.size.spacing.small} ${tokens.size.spacing.medium}`,
  },
  cardRow: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  radio: {
    flex: '1 0 auto',
    maxWidth: '90%',
  },
  radioButton: {
    cursor: 'pointer',
  },
  label: {
    position: 'relative',
    top: tokens.size.spacing.xxxSmall,
    width: '100%',
    fontSize: tokens.size.font.body,
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    marginLeft: tokens.size.spacing.xSmall,
    cursor: 'pointer',
  },
  labelSelected: {
    fontWeight: tokens.font.weight.bold,
  },
  deleteContainer: {
    flex: '1 0 auto',
  },
  deleteLink: {
    float: 'right',
    marginTop: -tokens.size.spacing.xxxSmall,
  },
  deleteLinkHidden: {
    visibility: 'hidden',
  },
  shouldSaveContainer: {
    display: 'block',
    position: 'relative',
    top: -tokens.size.spacing.xxxSmall,
    left: tokens.size.spacing.xxLarge,
  },
  shouldSave: {
    label: {
      position: 'relative',
      top: tokens.size.spacing.xxxSmall,
      color: tokens.color.neutral.grey4,
      fontSize: tokens.size.font.caption,
      fontStyle: 'italic',
    },
  },
  submitButton: {
    marginTop: tokens.size.spacing.xxSmall,
    display: 'block',
    width: '99%',
    marginLeft: '0.5%',
  },
}))

const defaultLang = {
  choosePayment: 'Choose a payment',
  deleteCard: 'Delete',
  saveInProfile: 'Save in Profile',
  submitCard: 'Proceed with Checkout',
}

const CreditCardList = props => {
  const {
    id,
    i18n,
    cards,
    selectedCardExtId,
    showSaveCardToggle,
    onSelectCard,
    onDeleteCard,
    onSubmitCard,
    onUpdateCard,
  } = props

  const styles = useStyles(props)
  const lang = { ...defaultLang, ...i18n }
  const { choosePayment, deleteCard, saveInProfile, submitCard } = lang
  const selected = cards.findIndex(({ ext_id }) => ext_id === selectedCardExtId)
  const radioBoxName = `creditCardList_${id}`

  return (
    <form id={id} data-test="creditCardList" css={styles.container}>
      <div css={styles.title}>{choosePayment}</div>

      {cards.map(({ ext_id, isNew, last_four_digits, provider, shouldSave }, idx) => {
        const isSelected = idx === (selected !== -1 ? selected : 0)
        const label = `${provider} ****${last_four_digits}`

        return (
          <div key={ext_id} css={[styles.card, isSelected && styles.cardSelected]}>
            <span css={styles.cardRow}>
              <span title={label} css={styles.radio}>
                <input
                  type="radio"
                  id={`radio_${ext_id}`}
                  name={radioBoxName}
                  checked={isSelected}
                  onClick={() => onSelectCard(ext_id)}
                  css={styles.radioButton}
                />
                <label
                  htmlFor={`radio_${ext_id}`}
                  title={label}
                  css={[styles.label, isSelected && styles.labelSelected]}
                >
                  {label}
                </label>
              </span>
              <span css={styles.deleteContainer}>
                <Button
                  variant="link"
                  size="small"
                  onClick={() => onDeleteCard(ext_id)}
                  css={[styles.deleteLink, isSelected && styles.deleteLinkHidden]}
                >
                  <span css={styles.label}>{deleteCard}</span>
                </Button>
              </span>
            </span>
            {showSaveCardToggle && isNew && (
              <span css={styles.shouldSaveContainer}>
                <Checkbox
                  id={`shouldSave_${ext_id}`}
                  checked={!!shouldSave}
                  onChange={e => onUpdateCard(ext_id, { saveInProfile: e.target.checked })}
                  inputSize="small"
                  styles={styles.shouldSave}
                >
                  {saveInProfile}
                </Checkbox>
              </span>
            )}
            {isSelected && (
              <Button onClick={() => onSubmitCard(ext_id)} css={styles.submitButton}>
                {submitCard}
              </Button>
            )}
          </div>
        )
      })}
    </form>
  )
}

CreditCardList.propTypes = {
  /** List of cards */
  cards: PropTypes.arrayOf(CARD_PROPS).isRequired,
  /** Language object for overrides */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Id for this component - required */
  id: PropTypes.string.isRequired,
  /** When the delete link for a card is pressed: onDelete(extId) */
  onDeleteCard: PropTypes.func.isRequired,
  /** When a card is selected via a radio box toggle: onSelect(extId) */
  onSelectCard: PropTypes.func.isRequired,
  /** When the submit button for a card is pressed: onSubmit(extId) */
  onSubmitCard: PropTypes.func.isRequired,
  /** When the status of a card is updated, e.g. saveInProfile: onCardUpdate(extId, stateUpdate) */
  onUpdateCard: PropTypes.func.isRequired,
  /** Selected card ExtId */
  selectedCardExtId: PropTypes.string,
  /** Whether to show the save card option */
  showSaveCardToggle: PropTypes.bool,
}

export default memo(CreditCardList)
