import angular from 'angular'
import template from './productCommentsV2.html'

export default angular
  .module('ProductCommentsV2ComponentModule', [])
  .directive('productCommentsV2', function () {
    return {
      scope: {
        item: '=',
        productCommentCallback: '&',
        productShowComment: '=',
        disableInput: '<?',
      },
      template,
      controller(
        $scope,
        analytics,
        cartService,
        config,
        lang,
        mobileService,
        oneCartService,
        utilService
      ) {
        Object.assign($scope, {
          lang,
          config,
          cartService,
          util: utilService,
          mobile: mobileService,
          tempComment: $scope.item.comment || '',
        })

        $scope.removeNote = () => {
          $scope.item.comment = ''
          $scope.productShowComment = false
          $scope.productCommentCallback($scope.item)
        }

        $scope.onCancel = () => {
          $scope.productShowComment = false
          $scope.tempComment = ''
        }

        $scope.onSaveNote = note => {
          $scope.item.comment = note
          $scope.productCommentCallback($scope.item)
          $scope.productShowComment = false
          analytics.logEvent('product save comment update', 'blur')
        }

        $scope.disableProductComments = () => {
          return (
            $scope.disableInput ||
            $scope.config.disableInput.item_comments ||
            $scope.item.product_config
          )
        }

        $scope.hideProductComments = () => !!$scope.item.product_config
      },
    }
  })
