import angular from 'angular'

export default angular
  .module('OfferDetailsModalControllerModule', [])
  .controller(
    'OfferDetailsModalController',
    function ($controller, $scope, auth, cartService, mobileService, offer) {
      $controller('OfferDetailsController', {
        $scope,
        offer,
      })
      $scope.isModal = true
      $scope.auth = auth
      $scope.cartService = cartService
      $scope.mobile = mobileService
    }
  )
  .run($templateCache => {
    $templateCache.put('offerDetails.html', require('../../offers/offerDetails.html'))
  })
