import angular from 'angular'
import template from './offerProgress.html'

import { CONDITION_TYPES, OFFER_TYPES, ITEM_CONDITION_TYPES } from '../../../constants/offers'

export default angular
  .module('OfferProgressComponentModule', [])
  .directive('offerProgress', $filter => {
    return {
      scope: {
        offerProgressData: '=',
        openOfferDetailsModal: '&?',
      },
      template,
      controller($scope, lang) {
        /*
         * Language constants
         */
        const {
          getTheDeal,
          itemConditionType,
          cartConditionType,
          rewardType,
          complete,
        } = lang.values.offersContent
        const { spendMoreToQualify, addItemsToQualify, addMoreToQualify } = itemConditionType
        const { spendMoreToComplete } = cartConditionType
        const { multipleItemsForReward, singleItemForReward } = rewardType
        const { clipThisCoupon, offerCompleted, offerTimes } = complete

        /*
         * OFFER LABELS
         */
        // eslint-disable-next-line complexity
        $scope.$watch('offerProgressData', value => {
          $scope.offerCount = null // this handles when offers are removed
          $scope.label = getTheDeal

          if (value) {
            const { reward } = value
            const conditionType = value.condition.type
            const conditionCurrent = parseFloat(value.condition.current_value) || 0
            let conditionMax = parseInt(value.condition.max_value, 10) || 0

            if (
              value.offer &&
              value.offer.additional_cdns &&
              value.offer.additional_cdns.length > 1
            ) {
              conditionMax = value.offer.additional_cdns.reduce(
                (accumulator, cdn) => accumulator + cdn.products.quantity,
                0
              )
            }

            const conditionDifference = conditionMax - conditionCurrent
            const rewardDifference = reward ? reward.max_value - reward.current_value : 0

            if (ITEM_CONDITION_TYPES.includes(conditionType) && conditionDifference > 0) {
              const offerConditionData = value.offer.condition[conditionType]

              if (offerConditionData?.min_spend) {
                const conditionMax = parseFloat(offerConditionData.min_spend) || 0
                const conditionDifference = (conditionMax - conditionCurrent).toFixed(2)
                $scope.label = lang.template(spendMoreToQualify, { conditionDifference })
              } else {
                const conditionMinWeight = offerConditionData?.min_weight
                const minWeightStr = conditionMinWeight
                  ? `(${conditionMinWeight}${offerConditionData.uom})`
                  : ''
                const itemTitle = $filter('pluralize')('item', conditionDifference)
                $scope.label = lang.template(addMoreToQualify, {
                  conditionDifference,
                  itemTitle,
                  minWeightStr,
                })
              }
            }

            if (conditionType === CONDITION_TYPES.CART && conditionDifference > 0) {
              $scope.label = lang.template(spendMoreToComplete, {
                conditionDifference: conditionDifference.toFixed(2),
              })
            }

            // Detect collection missing condition data
            if (conditionType === CONDITION_TYPES.COLLECTION && conditionCurrent === 0) {
              $scope.label = addItemsToQualify
            }

            // Show how many reward products need to be added to redeem the offer
            if (
              conditionType !== CONDITION_TYPES.NONE &&
              conditionDifference <= 0 &&
              rewardDifference > 0
            ) {
              $scope.label =
                rewardDifference > 1
                  ? lang.template(multipleItemsForReward, { rewardDifference })
                  : singleItemForReward
            }

            // Label offers as completed, with an optional count if completed more than once
            if (conditionType === CONDITION_TYPES.NONE || value.completion_count > 0) {
              if (value.offer.offer_type === OFFER_TYPES.COUPON && value.offer.clipped === false) {
                $scope.label = clipThisCoupon
              } else {
                const count = value.completion_count
                $scope.label = lang.template(offerCompleted, {
                  text: count > 1 ? `${count} ${offerTimes}` : '',
                })
              }
            }

            $scope.offerCount = value.completion_count ? value.completion_count : ''

            let progress = 0
            if (value.completion_count > 0 || ($scope.offerCount && conditionCurrent === 0)) {
              progress = 100
            } else {
              progress = Math.min(
                Math.max(parseInt((conditionCurrent / (conditionMax || 1)) * 100, 10), 0),
                100
              )
            }

            $scope.progress = `${progress}%`

            // Set values to determine if we are displaying completed, conditional or the unclipped state for the progress indicator.
            $scope.completed = value.completion_count > 0
            $scope.conditional = $scope.progress === '100%' && !$scope.completed

            if (
              $scope.offerProgressData.offer_type === OFFER_TYPES.COUPON ||
              ($scope.offerProgressData.offer &&
                $scope.offerProgressData.offer.offer_type === OFFER_TYPES.COUPON)
            ) {
              $scope.unclipped = $scope.completed && $scope.offerProgressData.clipped === false
            }
          }
        })
      },
    }
  })
