import angular from 'angular'

export default angular
  .module('manualUpsellServicesModule', [])
  .service('manualUpsellService', function manualUpsellService(Restangular, auth, config) {
    const service = this

    service.manualUpsellProducts = null

    service.getUpsellProducts = (forceReload = false) => {
      if (!forceReload && service.manualUpsellProducts) {
        return service.manualUpsellProducts
      }
      const authMode = auth.user.status === 'trial' ? 'trial' : 'loggedIn'
      const sort = config.defaultSort[authMode] ? config.defaultSort[authMode].shop : 'score'

      return Restangular.all('cart')
        .all(`manual_upsell?sort=${sort}`)
        .getList()
        .then(products => (service.manualUpsellProducts = products))
        .catch(() => (service.manualUpsellProducts = []))
    }
  })
