import angular from 'angular'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import policyTemplate from './views/policy.html'

export default angular
  .module('PolicyServiceModule', [])
  .service(
    'policyService',
    function policyService(
      $injector,
      $rootScope,
      $state,
      $uibModal,
      $window,
      auth,
      config,
      lang,
      legacyModalService
    ) {
      'ngInject'

      const service = this

      service.open = function () {
        legacyModalService.closeOpenModals()

        const modal = $uibModal.open({
          template: policyTemplate,
          controller() {},
          controllerAs: '$ctrl',
          keyboard: true,
          backdrop: 'static',
        })

        return modal.result
      }

      // Listen for events outside of Angular app
      // We need a better way to do this
      // Going to avoid rewriting/introducing more code for now
      eventEmitter.on(EVENT_TYPES.MODAL_OPEN_POLICY, ({ tab }) => {
        service.open(tab)
      })
    }
  )
