import angular from 'angular'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import template from './registrationDropdown.html'

export default angular
  .module('UnataRegistrationDropdownModule', [])
  .component('unataRegistrationDropdown', {
    controller(config, $scope, $state, express, utilService, registrationService) {
      $scope.config = config
      $scope.express = express
      $scope.loadImage = utilService.loadAssetImage
      $scope.customIcon = $scope.config.icExpress.placements.userDropdown.icon
      $scope.showLogin = () => {
        registrationService.open(
          'login',
          null,
          null,
          null,
          null,
          null,
          LOGIN_CONTEXTS.REGISTRATION_DROPDOWN
        )
      }

      eventEmitter.on(EVENT_TYPES.MODAL_OPEN_REGISTRATION_LOGIN, $scope.showLogin)

      $scope.$on('$destroy', () => {
        eventEmitter.off(EVENT_TYPES.MODAL_OPEN_REGISTRATION_LOGIN, $scope.showLogin)
      })
    },
    template,
  })
