import angular from 'angular'
import { captureException, captureMessage } from '@sentry/browser'

/**
 * How to enable lazy-loading for a AngularJS component or directive:
 * 1. Remove the corresponding static `import` statement from app/angular/components/**\/index.js.
 * 2. Add `lazy-loading` attribute to all elements associated with a corresponding component or directive.
 * 3. Add an entry to `lazyLoadables`:
 * 'component-or-directive-name': {
 *   importModule: () => import(/* webpackChunkName: "componentOrDirectiveName" *\/ '.../componentOrDirectiveName'),
 *   [indicatorProps: { [optional LazyLoadingIndicator props] },]
 * },
 */
const lazyLoadables = {
  'register-form': {
    importModule: () =>
      import(
        /* webpackChunkName: "registerForm" */ '@app/angular/components/shared/registerForm/registerForm'
      ),
  },
  'register-form-improved': {
    importModule: () =>
      import(
        /* webpackChunkName: "registerFormImproved" */ '@app/angular/components/shared/registerFormImproved/registerFormImproved'
      ),
  },
  'shared-login': {
    importModule: () =>
      import(
        /* webpackChunkName: "sharedLogin" */ '@app/angular/components/shared/util/sharedLogin'
      ),
  },
}

const angularModule = angular
  .module('lazyLoadingServicesModule', [])
  .config(function ($compileProvider) {
    angularModule.$compileProvider = $compileProvider
    angularModule.lazyLoadedNames = new Set()
  })
  .directive('lazyLoading', function ($compile) {
    return {
      async link(scope, elements) {
        const element = elements[0]

        if (element.childElementCount > 0) {
          return
        }

        const getName = name => (name in lazyLoadables ? name : undefined)
        let name = getName(element.tagName.toLowerCase())
        if (!name) {
          name = Object.values(element.attributes)
            .map(({ name }) => name)
            .find(name => getName(name))
        }
        if (!name) {
          const message = `No known lazy-loadable directive or component for the element: ${element.outerHTML}`
          console.warn(message)
          captureMessage(message)
          return
        }

        let isError

        if (!angularModule.lazyLoadedNames.has(name)) {
          angularModule.lazyLoadedNames.add(name)

          try {
            const { importModule, indicatorProps } = lazyLoadables[name]

            const attributes = Object.entries(indicatorProps ?? {})
              .map(([name, value]) => (typeof value === 'boolean' ? name : `${name}=${value}`))
              .join(' ')
            const indicator = $compile(`<react-lazy-loading-indicator ${attributes} />`)(scope)[0]
            element.appendChild(indicator)

            const module = await importModule()
            module.default._invokeQueue.forEach(item => {
              const [type, [name, func]] = item.slice(item.length - 2)
              angularModule.$compileProvider[type](name, func)
            })
          } catch (error) {
            console.error(error)
            captureException(error)

            const alert = $compile(
              `<alert type="warn-light" disable-border="true" small-text="true">Failed to load module.</alert>`
            )(scope)[0]
            while (element.firstChild) {
              element.firstChild.remove()
            }
            element.appendChild(alert)
          }
        }

        if (!isError) {
          $compile(element)(scope)
          scope.$applyAsync()
        }
      },
    }
  })

export default angularModule
