import angular from 'angular'
import { DEFAULT_TIMESLOT_DATE_FORMAT } from '@app/angular/constants/timeslots'
import { utils as timeslotUtils } from '@instacart/enterprise-timeslots'
import { multiShoppingContextsAvailableSelector } from '@app/services/shoppingContext/selectors'
import {
  ctxSubTitleSelector,
  isCtxChangeDisabledSelector,
  mobileShoppingModeTitleSelector,
} from '@app/react/components/contextSelector/selectors'
import template from './views/shoppingSelectorNav.html'

export default angular
  .module('UnataShoppingSelectorNavModule', [])
  .component('unataShoppingSelectorNav', {
    controller(
      $ngRedux,
      analytics,
      auth,
      catalogCategoriesService,
      config,
      contextSelectorButtonService,
      lang,
      mobileService,
      reservationService,
      shoppingContextService,
      shoppingSelectorService,
      uibModalAriaLabelService,
      utilService,
      $state,
      $rootScope,
      $scope
    ) {
      this.auth = auth
      this.config = config
      this.isMobile = mobileService.isMobile
      this.lang = lang
      this.reservationService = reservationService
      this.$state = $state
      this.utilService = utilService
      this.searchIconVisible = false

      this.triageSelectorButtonClicks = contextSelectorButtonService.triageSelectorButtonClicks
      this.openMobileContextSelector = contextSelectorButtonService.openMobileContextSelector

      const mapStateToService = state => ({
        multiShoppingContextsAvailable: multiShoppingContextsAvailableSelector(state),
      })
      $ngRedux.connect(mapStateToService)(this)

      this.shoppingModeTitle = mobileShoppingModeTitleSelector.resultFunc(
        this.config,
        this.auth.user
      )
      this.isDropdownDisabled = isCtxChangeDisabledSelector.resultFunc(
        this.multiShoppingContextsAvailable,
        this.auth.user
      )
      this.subTitle = ctxSubTitleSelector.resultFunc(this.config, this.lang.values, this.auth.user)

      this.loadImg = image => {
        return require(`assets/images/${image}`)
      }

      /**
       * We are generating the content for the react reservation/timeslot button here to simplify
       * the interface for the react context selector.
       * TODO When an enterprise-services reservation service has been created, it will probably make
       * sense to pull the reservation service directly into the react context selector component and
       * move this logic inside of the component.
       */
      const generateReservationTimeslotContent = () => {
        const content = {
          prependText: '',
          bodyText: '',
        }

        content.bodyText = String(
          timeslotUtils.getMainLabel(this.reservationService.displayReservation, {
            verboseLevel: 0,
            returnLabel: false,
            format: this.reservationService.reservation
              ? this.config.apiDateFormat
              : DEFAULT_TIMESLOT_DATE_FORMAT,
          })
        )

        content.prependText = this.reservationService.previewTimeslot
          ? this.lang.values.timeslotAvailable
          : this.lang.values.on
        this.reservationTimeslotContent = content
      }

      // Generate the reservation timeslot content every time the Angular component loads.
      generateReservationTimeslotContent()

      // Regenerate the reservation timeslot content whenever displayReservation or previewTimeslot has been changed.
      $scope.$watch(
        () => {
          return this.reservationService.displayReservation
        },
        () => {
          generateReservationTimeslotContent()
        }
      )

      $scope.$watch(
        () => {
          return this.reservationService.previewTimeslot
        },
        () => {
          generateReservationTimeslotContent()
        }
      )

      // Property for disabling the react context selector when Instacart/Unata Order is being modified.
      $scope.$watch(
        () => $rootScope.modifyingInstacartOrder || $rootScope.modifyingOrder,
        () => {
          this.disableContextSelector =
            $rootScope.modifyingInstacartOrder || $rootScope.modifyingOrder
        }
      )

      $scope.$on('hideSearch', (event, hideSearch) => {
        this.searchIconVisible = hideSearch
      })
    },

    template,

    controllerAs: '$ctrl',
  })
