import angular from 'angular'
import { USER_STATUSES } from '@app/angular/constants/user'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import {
  SHOPPING_SELECTOR_STAGES,
  TRANSITIONS,
} from '@app/angular/constants/shoppingSelectorStages'
import { SOURCES as TIMESLOT_SOURCES } from '@app/angular/constants/timeslots'
import { URL_OVERRIDES } from '@app/angular/services/modules/userOverridesV2.service'
import { hasRxItem } from '@instacart/enterprise-services/shopping-list/_common/utils'
import unableToChangeToPickupRx from '../views/unableToChangeToPickupRx.html'

export default angular
  .module('ContextSelectorControllerComponentModule', [])

  // Shopping context controller

  .controller(
    'ContextSelectorController',
    function (
      $q,
      $rootScope,
      $scope,
      $state,
      $timeout,
      analytics,
      auth,
      cartListTransferService,
      cartService,
      compareCartService,
      config,
      confirmationModal,
      eComLightService,
      fulfillmentNotAvailableModalService,
      lang,
      mobileService,
      reservationService,
      shoppingContextService,
      shoppingSelectorService,
      userOverridesV2Service
    ) {
      $scope.auth = auth
      $scope.lang = lang
      $scope.shopContextIntent = auth.getContext().intent
      $scope.mobile = mobileService
      $scope.cartService = cartService
      $scope.reservationService = reservationService
      $scope.modalTransition = false
      $scope.initialContextIntent = auth.getContext().intent
      $scope.inCheckout = $state.includes('main.checkout')

      /**
       * Checks for V2 overrides for shopping mode
       * @param {function} [deregisterListener] - Optional: Function to stop listening for auth changes
       */
      function checkOverrides(deregisterListener) {
        if (deregisterListener) deregisterListener()
        const { [URL_OVERRIDES.MODE]: overrideMode } = userOverridesV2Service.selectOverrides()
        if (Object.values(SHOPPING_MODES).includes(overrideMode)) {
          $scope.shoppingContextSubmit(overrideMode)
          userOverridesV2Service.deleteOverride(URL_OVERRIDES.MODE)
          userOverridesV2Service.removeNonApplicableOverrides()
        }
      }

      this.$onInit = () => {
        if (auth.getContext()?.intent !== undefined) {
          checkOverrides()
        } else {
          userOverridesV2Service.onAuthReady(checkOverrides)
        }
      }

      $scope.shopContextIntents = [
        {
          name: SHOPPING_MODES.DELIVERY,
          displayOrder: config.shoppingSelector.delivery.displayOrder,
          classIcons: 'icon-delivery',
          ariaLabel: lang.aria.switchDelivery,
          isActive: $scope.shopContextIntent === SHOPPING_MODES.DELIVERY,
          visible:
            (!auth.user.store.is_b2b && config.shoppingSelector.delivery.exists) ||
            (config.shoppingSelector.delivery.exists &&
              auth.user.store.is_b2b &&
              auth.userStoreSupportsDelivery()),
        },
        {
          name: SHOPPING_MODES.PICKUP,
          displayOrder: config.shoppingSelector.pickup.displayOrder,
          classIcons: 'icon-pickup icon-card',
          ariaLabel: lang.aria.switchPickup,
          isActive:
            $scope.shopContextIntent === SHOPPING_MODES.PICKUP && !eComLightService.inEComLightMode,
          visible: config.shoppingSelector.pickup.exists,
        },
        {
          name: SHOPPING_MODES.INSTORE_LIST,
          displayOrder: config.shoppingSelector.instore.displayOrder,
          classIcons: 'icon-list',
          ariaLabel: lang.aria.switchInstoreList,
          isActive: $scope.shopContextIntent === SHOPPING_MODES.INSTORE_LIST,
          visible: config.shoppingSelector.instore.exists && !auth.user.store.is_b2b,
        },
        {
          name: SHOPPING_MODES.CATERING,
          displayOrder: config.shoppingSelector.catering.displayOrder,
          classIcons: 'icon-catering icon-card',
          ariaLabel: lang.aria.switchCatering,
          isActive:
            $scope.shopContextIntent === SHOPPING_MODES.PICKUP && eComLightService.inEComLightMode,
          visible: config.shoppingSelector.catering.exists,
        },
      ]

      $scope.visibleShopContextIntents = $scope.shopContextIntents.filter(ctx => ctx.visible)

      const shouldHideStoreSelector = {
        [SHOPPING_MODES.PICKUP]: config?.shoppingContextProcess?.pickup?.hideStoreSelector,
        [SHOPPING_MODES.DELIVERY]: config?.shoppingContextProcess?.delivery?.hideStoreSelector,
        [SHOPPING_MODES.INSTORE_LIST]: config?.shoppingContextProcess?.instore?.hideStoreSelector,
      }

      const shouldHideTimeslotSelector = {
        [SHOPPING_MODES.PICKUP]: config?.shoppingContextProcess?.pickup?.hideTimeslot,
        [SHOPPING_MODES.DELIVERY]: config?.shoppingContextProcess?.delivery?.hideTimeslot,
        [SHOPPING_MODES.INSTORE_LIST]: config?.shoppingContextProcess?.instore?.hideTimeslot,
      }

      const _getPickupTransitionState = initialContext => {
        const userHasStore = auth.user?.store?.id
        const isTimeslotSelectorHidden =
          (auth.user.status === USER_STATUSES.TRIAL &&
            config.hideTimeslotReservationFromTrialUsers) ||
          shouldHideTimeslotSelector.pickup
        const isStoreSelectorHidden =
          userHasStore && auth.userStoreSupportsPickup() && shouldHideStoreSelector.pickup

        if (isStoreSelectorHidden) {
          if (isTimeslotSelectorHidden) {
            return {
              transition: TRANSITIONS.CART_COMPARE,
              destination: SHOPPING_SELECTOR_STAGES.CLOSE,
            }
          }

          return {
            transition: TRANSITIONS.CART_COMPARE,
            destination: SHOPPING_SELECTOR_STAGES.TIMESLOT,
          }
        }

        // If the user is coming from list or catering and the current store supports pickup, skip the store selection
        // This behaviour is optional
        const allowSkippingStoreSelection =
          config?.shoppingContextProcess?.instore?.allowSkippingStoreSelection

        if (
          allowSkippingStoreSelection &&
          (initialContext === SHOPPING_MODES.INSTORE_LIST ||
            initialContext === SHOPPING_MODES.CATERING) &&
          auth.user?.store?.has_pickup
        ) {
          // if timeslot selection is disabled, close the modal
          const destination = isTimeslotSelectorHidden
            ? SHOPPING_SELECTOR_STAGES.CLOSE
            : SHOPPING_SELECTOR_STAGES.TIMESLOT

          return {
            transition: TRANSITIONS.CART_COMPARE,
            destination,
          }
        }

        // Store selector is visible
        return {
          transition: TRANSITIONS.SHOPPING_SELECTOR,
          destination: SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
        }
      }

      const _getCateringTransitionState = () => {
        if (
          auth.user?.store?.has_catering &&
          auth.getContext().shopping_mode !== SHOPPING_MODES.CATERING
        ) {
          return {
            transition: TRANSITIONS.CART_COMPARE,
            destination: SHOPPING_SELECTOR_STAGES.CLOSE,
          }
        }

        return {
          transition: TRANSITIONS.SHOPPING_SELECTOR,
          destination: SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
        }
      }

      const _getDeliveryTransitionState = () => {
        const userHasZip = auth.user && auth.user.context && auth.user.context.deliveryPostalCode
        const isStoreSelectorHidden = userHasZip && shouldHideStoreSelector.delivery
        const isTimeslotSelectorHidden =
          (auth.user.status === USER_STATUSES.TRIAL &&
            config.hideTimeslotReservationFromTrialUsers) ||
          shouldHideTimeslotSelector.delivery

        if (isStoreSelectorHidden) {
          if (isTimeslotSelectorHidden) {
            return {
              transition: TRANSITIONS.CART_COMPARE,
              destination: SHOPPING_SELECTOR_STAGES.CLOSE,
            }
          }

          return {
            transition: TRANSITIONS.CART_COMPARE,
            destination: SHOPPING_SELECTOR_STAGES.TIMESLOT,
          }
        }

        // Store selection (address) is visible
        return {
          transition: TRANSITIONS.SHOPPING_SELECTOR,
          destination: SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
        }
      }

      const _getInstoreListTransitionState = initialContext => {
        const userHasStore = auth.user && auth.user.store && auth.user.store.id

        /**
         * If the user is currently in *instore* list mode, and selects it again,
         * we open the store selector
         */
        if (initialContext === SHOPPING_MODES.INSTORE_LIST) {
          return {
            transition: TRANSITIONS.SHOPPING_SELECTOR,
            destination: SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
          }
        }

        if (userHasStore && $scope.inCheckout) {
          return {
            transition: TRANSITIONS.CART_COMPARE,
            destination: 'main.home',
          }
        }

        if (shouldHideStoreSelector.instore) {
          return {
            transition: TRANSITIONS.CART_COMPARE,
            destination: SHOPPING_SELECTOR_STAGES.CLOSE,
          }
        }

        /**
         * TODO: uncomment this block when the BE returns a has_list or similar attribute
         * in the user store. Right now, let's assume all stores supports list mode
         */
        // if (initialContext === SHOPPING_MODES.PICKUP && auth.user.store.has_list) {
        //   return {
        //     transition: TRANSITIONS.CART_COMPARE,
        //     destination: SHOPPING_SELECTOR_STAGES.CLOSE,
        //   }
        // }

        // return {
        //   transition: TRANSITIONS.SHOPPING_SELECTOR,
        //   destination: SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
        // }

        return {
          transition: TRANSITIONS.CART_COMPARE,
          destination: SHOPPING_SELECTOR_STAGES.CLOSE,
        }
      }

      function determineShoppingContextTransition() {
        const updatedContext = shoppingContextService.selectedFulfillmentType
        const transitionStateMap = {
          [SHOPPING_MODES.PICKUP]: _getPickupTransitionState,
          [SHOPPING_MODES.DELIVERY]: _getDeliveryTransitionState,
          [SHOPPING_MODES.INSTORE_LIST]: _getInstoreListTransitionState,
          [SHOPPING_MODES.CATERING]: _getCateringTransitionState,
        }

        // When user changed to/from eCom Light mode, set the requiredRedirect on the eComLightService.
        // When the modal 'flow' is complete send them to "Home" which will trigger the correct redirects.
        if (eComLightService.inEComLightMode) {
          if ($state.is('main.home')) {
            eComLightService.requiresRedirect = false
          } else {
            eComLightService.requiresRedirect = true
          }
        }

        const initialContext = shoppingContextService.getIntentFromContext()
        const transitionState = transitionStateMap[updatedContext](initialContext)

        return (
          transitionState || {
            transition: TRANSITIONS.SHOPPING_SELECTOR,
            destination: SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
          }
        )
      }

      const _handleCartCompareConfirmation = transferData => {
        const { shopping_mode: initialFulfillmentType } = auth.getContext()
        const { selectedFulfillmentType } = shoppingContextService
        const fulfillmentTypesShowTransferModal = [
          SHOPPING_MODES.INSTORE_LIST,
          SHOPPING_MODES.CATERING,
        ]

        const showCartListTransfer =
          // We should only show the transfer modal if we are switching in/out of catering or instore list
          [initialFulfillmentType, selectedFulfillmentType].some(type =>
            fulfillmentTypesShowTransferModal.includes(type)
          ) &&
          initialFulfillmentType !== selectedFulfillmentType &&
          (transferData.numTotal > 0 || cartService.countItems() > 0)

        if (!showCartListTransfer) {
          return $q.resolve()
        }

        $scope.$parent.setShoppingSelectorStage(SHOPPING_SELECTOR_STAGES.CART_TO_LIST_MIGRATION)

        return cartListTransferService.initializeTransfer({
          initialContext: initialFulfillmentType,
          updatedContext: selectedFulfillmentType,
          transferData,
        })
      }

      function transitionToNextStep(transition, destination) {
        if (transition === TRANSITIONS.SHOPPING_SELECTOR) {
          $scope.$parent.setShoppingSelectorStage(destination)
        } else if (transition === TRANSITIONS.STATE_GO) {
          $state.go(destination)
        } else if (transition === TRANSITIONS.CLOSE_MODAL) {
          $scope.$close()
        } else if (transition === TRANSITIONS.CART_COMPARE) {
          const storeId = auth.user?.store?.id
          const { deliveryPostalCode } = auth.user.context
          const cateringConfig = config.shoppingSelector.catering
          const intent = shoppingContextService.selectedFulfillmentType

          compareCartService
            .compareCart(true, 'store', {
              store_id: storeId,
              fulfillment_type: intent,
              delivery_postal_code: deliveryPostalCode,
            })
            .then(_handleCartCompareConfirmation)
            .then(() => auth.setIntent(intent))
            .then(cartService.fetch)
            .then(() => {
              if (shoppingContextService.selectedFulfillmentType === SHOPPING_MODES.CATERING) {
                eComLightService.setMode(true)
                $state
                  .go(cateringConfig?.redirectTo, cateringConfig?.redirectParams)
                  .then($rootScope.reloadState)
              } else {
                eComLightService.setMode(false)
              }

              if (destination === SHOPPING_SELECTOR_STAGES.CLOSE) {
                return $scope.$close()
              }

              if (destination && destination.match(/^[a-z]+\.[a-z]/i)) {
                return $state.go(destination)
              }

              $scope.$parent.setShoppingSelectorStage(destination)
            })
            .catch(error => {
              console.error('Context selector error:', error)
              $scope.$close()
            })
        }
      }

      function proceedToNextShoppingSelectorStage(userCameFromTransfer) {
        const { transition, destination } = determineShoppingContextTransition(userCameFromTransfer)
        // Only show modal transition if we're going to store or timeslot selector in mobile and we didn't come from transfer
        if (
          mobileService.isMobile &&
          transition === TRANSITIONS.SHOPPING_SELECTOR &&
          (destination === SHOPPING_SELECTOR_STAGES.TIMESLOT ||
            destination === SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
        ) {
          $scope.modalTransition = true
          $timeout(() => {
            transitionToNextStep(transition, destination)
          }, 700)
        } else {
          $scope.modalTransition = false
          transitionToNextStep(transition, destination)
        }
      }

      $scope.shoppingContextSubmitNoChecks = updatedContext => {
        $scope.shopContextIntent = updatedContext

        // TODO find a cleaner way to make sure we don't have an invalid reservation for the fulfillment type
        if (
          config.eComLight.enabled &&
          ((updatedContext === SHOPPING_MODES.CATERING &&
            reservationService?.reservation?.timeslot?.source === TIMESLOT_SOURCES.IC) ||
            (updatedContext !== SHOPPING_MODES.CATERING &&
              reservationService?.reservation?.timeslot?.source === TIMESLOT_SOURCES.INTERNAL))
        ) {
          reservationService.deleteReservation()
        }

        shoppingContextService.setSelectedFulfillmentType(updatedContext)
        proceedToNextShoppingSelectorStage()
      }

      $scope.shoppingContextSubmit = updatedContext => {
        if (hasRxItem(cartService.cart.items) && SHOPPING_MODES.DELIVERY !== updatedContext) {
          analytics.logSegmentEvent('rx.context_switching_triggered', {
            source: 'shopping_context',
          })

          confirmationModal.open({}, unableToChangeToPickupRx).then(proceed => {
            if (proceed) {
              cartService.removeRxItems().then(() => {
                $scope.shoppingContextSubmitNoChecks(updatedContext)
              })
            } else {
              shoppingSelectorService.closeModal()
            }
            analytics.logSegmentEvent('rx.context_switched', {
              source: 'shopping_context',
              context_switched: proceed,
              rx_item_removed: proceed,
            })
          })
        } else {
          $scope.shoppingContextSubmitNoChecks(updatedContext)
        }
      }
    }
  )
