import angular from 'angular'
import template from './bannerForm.html'

const FORM_ID = 'banner'

class BannerForm {
  constructor($q, $scope, $timeout, auth, compareCartService, config, storesService) {
    Object.assign(this, {
      $q,
      $scope,
      $timeout,
      auth,
      bannerSlugsInZip: [],
      compareCartService,
      config,
      storesService,
      hasFetchedBanners: false,
    })

    // Cannot use ES6 class property initializers within a class.
    // Causes the minified version of the app to fail.
    this._handleFetchBanners = this._handleFetchBanners.bind(this)
  }

  $onInit() {
    // Watch for the section expanding
    this.$scope.$watch(
      () => this.editMode,
      () => {
        if (this.editMode && !this.hasFetchedBanners) {
          this.fetchBanners()
        }
      }
    )
  }

  _handleFetchBanners(banners) {
    this.hasFetchedBanners = true
    this.bannerSlugsInZip = banners.items
  }

  fetchBanners() {
    this.storesService
      .fetchBanners({ delivery_postal_code: this.address.postal_code })
      .then(this._handleFetchBanners)
      .catch(this.handleError)
  }

  enterEditMode() {
    this.edit({ step: FORM_ID })

    if (!this.hasFetchedBanners) {
      this.fetchBanners()
    }
  }

  selectBanner(bannerSlug) {
    this.storesService
      .fetch({
        delivery_postal_code: this.address.postal_code,
        retailer_slug: bannerSlug,
        show_delivery: true,
      })
      .then(stores => {
        if (stores.length > 0) {
          this.updateDeliveryStore(stores[0])
        } else {
          // This should not happen
          this.handleError({ message: this.config.lang.noDeliveryToPostalCodeError })
        }
      })
      .catch(this.handleError)
  }

  updateDeliveryStore(store) {
    const params = this.auth.getUrlParamsFromAddress(this.address)
    params.store_id = store.id
    params.delivery_address = this.address

    const contextParams = this.auth.getContextFromAddress(this.address)
    if (store.store_banner && store.store_banner.key) {
      contextParams.retailer_slug = store.store_banner && store.store_banner.key
    }

    return this.compareCartService
      .compareCart(true, 'store', params)
      .then(() => this.auth.setContext(contextParams))
      .then(() => this.changeHomeStore(store))
  }

  changeHomeStore(store) {
    // Update the store
    return this.auth
      .update({ store_id: store.id, has_changed_store: true })
      .then(() => this.auth.sendUserUpdatedStore(this.auth.user))
      .then(() => this.auth.sendAvailableStoreContexts(this.auth.user))
      .then(() => this.onChangeBanner())
  }
}

export default angular.module('BannerFormModule', []).component('bannerForm', {
  bindings: {
    address: '=',
    edit: '&',
    editMode: '<',
    hideChevron: '<',
    onChangeBanner: '&',
    userStore: '<',
  },
  template,
  controller: BannerForm,
})
