import _ from 'lodash'
import angular from 'angular'
import distance from '@turf/distance'
import { SHOPPING_MODES } from '@app/angular/constants/context'

import { prioritizeActiveStore } from '../../components/shoppingSelector/storeSelector/helpers'

export default angular
  .module('storeServicesModule', [])
  .service(
    'storesService',
    function storesService($filter, $http, $q, Restangular, config, unataCommService) {
      const service = this

      service.distanceUnits = config.useKMForStoreSelectorUnits ? 'kilometers' : 'miles'

      service.storeDistanceReferenceMap = {}

      service.preferredFulfillmentType = 'pickup'

      // Fetch a list of stores which are valid for our current configuration

      service.fetch = function (params) {
        params = params === undefined ? {} : params

        if (config.banner) {
          params.banner = config.banner
        }

        if (config.showEcommerce) {
          params.show_ecommerce = config.showEcommerce
        }

        const promise = Restangular.all('stores').getList(params)

        promise.then(stores => {
          service.stores = stores

          // TODO: Remove this when Integration step ensures this never happens.
          // Mutate store objects to ensure if `has_pickup` is `false`, that `show_pickup` is also `false`.
          // (This is important as the FE now only ever uses/checks `show_pickup` to determine if a store should be available in pickup or not.)
          // (Same applies to `has_delivery` and `show_delivery`.)
          _.forEach(service.stores, store => {
            if (!store.has_pickup && store.show_pickup) {
              store.show_pickup = false
            }
            if (!store.has_delivery && store.show_delivery) {
              store.show_delivery = false
            }
          })
        })

        return promise
      }

      service.fetchBanners = params => {
        return Restangular.one('stores/retailer_slugs').get(params)
      }

      /**
       * Validates the delivery for a given postal code
       *
       * @param { string } postalCode - The delivery postal code
       * @returns { Promise } - Promise that resolves to a errorObject with the following structure:
       *  {
       *    isValid: boolean,
       *    errorType?: string
       *  }
       */
      service.validateDelivery = (postalCode, userStoreId) => {
        const storesPayload = {
          delivery_postal_code: postalCode,
          show_delivery: true,
        }

        return service.fetch(storesPayload).then(stores => {
          // No stores available for the given postal code
          if (stores.length === 0) {
            return {
              isValid: false,
              errorType: 'NO_STORES_FOUND',
            }
          }

          const isStoreFound = !!_.find(stores, { id: userStoreId })

          if (isStoreFound) {
            return {
              isValid: true,
            }
          }

          return {
            isValid: false,
            errorType: 'STORE_NOT_IN_THE_LIST',
          }
        })
      }

      // Listening for a 'unata-get-stores' message from client
      unataCommService.listen(unataCommService.EventType.GETSTORES, () => {
        service.fetch().then(stores => {
          const simplifiedStores = stores.plain().map(store => service.simplifyStoreDetails(store))

          const payload = {
            stores: simplifiedStores,
          }

          unataCommService.send({ eventType: unataCommService.EventType.STORES, payload })
        })
      })

      // Listening for a 'unata-get-store' message from client
      unataCommService.listen(unataCommService.EventType.GETSTORE, event => {
        service.fetch().then(stores => {
          let store

          // External store id?
          if (event.data.payload.ext_id) {
            store = stores.plain().find(store => {
              return parseInt(store.ext_id, 10) === parseInt(event.data.payload.ext_id, 10)
            })
          }

          // Internal store id?
          if (event.data.payload.id) {
            store = stores.plain().find(store => {
              return parseInt(store.id, 10) === parseInt(event.data.payload.id, 10)
            })
          }

          // Set store and redirect if found
          if (store) {
            const payload = this.simplifyStoreDetails(store)
            unataCommService.send({ eventType: unataCommService.EventType.STORE, payload })
          }
        })
      })

      // Simplify the store details
      service.simplifyStoreDetails = store => {
        const fields = [
          'id',
          'ext_id',
          'href',
          'name',
          'phone_number',
          'address',
          'location',
          'retailer_store_id',
          'store_hours',
          'external_url',
        ]
        return _.pick(store, fields)
      }

      const createPoint = coordinates => ({
        type: 'Feature',
        geometry: {
          type: 'Point',
          coordinates,
        },
      })

      const distanceBetween = (a, b) => {
        const pointA = createPoint([Number(a.longitude), Number(a.latitude)])
        const pointB = createPoint([Number(b.longitude), Number(b.latitude)])

        return distance(pointB, pointA, { units: service.distanceUnits })
      }

      service.getStoresWithinRange = ({ stores, location, range } = {}) => {
        const storesInRange = []

        stores.forEach(store => {
          const distance = distanceBetween(store.location, location)

          service.storeDistanceReferenceMap[store.id] = distance

          if (distance >= 10 || distance === 0) {
            store.distance = distance.toFixed(0)
          } else {
            store.distance = distance.toFixed(1)
          }

          // Only list stores within a specific range
          if (distance <= range) {
            storesInRange.push(store)
          }
        })

        return storesInRange
      }

      const MATCHED_STORE_TYPES = {
        DEFAULT: 'DEFAULT',
        LOCATION_BASED: 'LOCATION_BASED',
        SEARCH_BASED: 'SEARCH_BASED',
      }
      service.MATCHED_STORE_TYPES = MATCHED_STORE_TYPES

      /**
       * This method filters a given array of stores using multiple params
       * @param {object} location - contains a latitude and longitude attributes
       * @param {array} allStores - stores to be filtered
       * @param {string} searchTerm - user search text
       * @param {string} intent - user current shopping intent
       * @param {string} storeID
       * @param {string} returnMatchedFor - would you like to center on default store or on store closest to user, ...
       *
       * @returns {object}
       *   contains an array of filtered *stores*
       *   and a *matchedStore* object
       */
      service.filterStores = ({
        location,
        allStores,
        searchTerm,
        intent,
        storeID,
        returnMatchedFor = MATCHED_STORE_TYPES.DEFAULT,
      }) => {
        let stores = []

        stores = allStores.filter(store => {
          // I know this is confusing, why do you match all when location exists? What does it prove?
          // For now this is just an optimization of previous code that is pointless (you can check history)
          let match = !!location

          // Include stores which match search term
          if (!searchTerm || $filter('filter')([store], searchTerm).length) {
            match = true
          }

          return match
        })

        // First, sort by distance
        let closestStore = null
        if (location) {
          stores = service.getStoresWithinRange({
            stores: allStores,
            location,
            range: config.storeSearchRange,
          })

          stores = _.sortBy(stores, store => {
            return service.storeDistanceReferenceMap[store.id]
          })
          closestStore = stores[0]
        }

        // Second, prioritize exact search matches
        stores = _.sortBy(stores, store => {
          return $filter('filter')([store], searchTerm).length ? 0 : 1
        })
        const matchBySearch = stores[0]

        // Third, prioritize stores that support pickup
        if (intent === SHOPPING_MODES.PICKUP) {
          stores = _.sortBy(stores, store => {
            return store.show_pickup ? 0 : 1
          })
        }

        // Lastly, prioritize stores that the user has previously shopped at
        stores = _.sortBy(stores, store => {
          return store.last_purchased === null ? 1 : 0
        })

        let matchedStore
        if (returnMatchedFor === MATCHED_STORE_TYPES.LOCATION_BASED) {
          matchedStore = closestStore
        } else if (returnMatchedFor === MATCHED_STORE_TYPES.SEARCH_BASED) {
          matchedStore = matchBySearch
        } else {
          matchedStore = stores[0]
        }

        stores = prioritizeActiveStore(storeID, stores)

        return {
          stores,
          matchedStore,
        }
      }

      service.getAddressCoordinates = address => {
        const deferred = $q.defer()
        $http
          .get(
            `https://api.mapbox.com/geocoding/v5/mapbox.places/${address}.json?country=us,ca&access_token=${config.mapboxglAccessToken}`
          )
          .then(response => {
            let location = {}
            if (response.data.features.length > 0) {
              location = {
                latitude: response.data.features[0].center[1],
                longitude: response.data.features[0].center[0],
              }
            }
            deferred.resolve(location)
          })
          .catch(err => {
            deferred.reject(err)
          })

        return deferred.promise
      }
    }
  )
