import angular from 'angular'

export default angular
  .module('InstacartCustomerLinkInputModule', [])
  .component('instacartCustomerLinkInput', {
    bindings: {
      setStage: '<',
    },
    controller(analytics, auth, instacartCustomerLinkService, lang, localStorageService) {
      const vm = this
      vm.lang = lang
      vm.auth = auth
      vm.linkStatus = lang.values.linkAccount
      vm.isLinking = false

      vm.linkAccount = () => {
        if (!vm.code) {
          return
        }
        vm.isLinking = true
        if (vm.isLinking) {
          vm.linkStatus = lang.values.linkingAccount

          instacartCustomerLinkService
            .verifyLinkCode(vm.code)
            .then(
              user => {
                auth.updateUser(user)
                // remove the linklater localstoreage entry
                localStorageService.deprecatedRemove('ic_linklater')
                analytics.logEvent('instacart account linked', 'linked')
                vm.setStage('confirm')
              },
              response => {
                vm.error = response.data ? response.data.message : response.message
              }
            )
            .finally(() => {
              vm.isLinking = false
              vm.linkStatus = lang.values.linkAccount
            })
        }
      }
      vm.resendCode = () => {
        instacartCustomerLinkService.sendLinkCode().then(
          () => {
            vm.message = lang.values.emailResent
          },
          response => {
            vm.error = response.data ? response.data.message : response.message
          }
        )
      }
    },
    template: `
        <div class="subheader">{{$ctrl.lang.values.linkAccount}}</div>
        <div class="instructions">
            <div>{{::$ctrl.lang.template($ctrl.lang.values.codeHasBeenSent, {emailAddress: $ctrl.auth.user.email})}}</div>
            <input type="tel" class="input" ng-pattern="/^[0-9]*$/" name="ic_code" ng-model="$ctrl.code"  required placeholder="{{::$ctrl.lang.values.enterCode}}" />
            <div ng-if="$ctrl.error" class="message error">
              <span class="icon-error" aria-hidden="true"></span>
              {{$ctrl.error}}
            </div>
            <div ng-if="$ctrl.message" class="message success">
              <span class="icon-checkmark-circle" aria-hidden="true"></span>
              {{$ctrl.message}}
            </div>
        </div>
        <div class="flexcontainer flexcoloumn">
            <button class="button" ng-class="{disabled:($ctrl.code.toString().length !== 6 || $ctrl.isLinking)}" ng-click="$ctrl.linkAccount()"><loading-dots show="$ctrl.isLinking"></loading-dots>{{$ctrl.linkStatus}}</button>
            <button class="link" ng-click="$ctrl.resendCode()">{{::$ctrl.lang.values.didntGetEmail}}</button>
        </div>
    `,
  })
