export default [
  'lang',
  'userService',
  (lang, userService) => {
    return {
      title: lang.values.loyaltyCartTitle,
      content: lang.values.loyaltyCartMessage,
      getValue: () =>
        lang.templateAsString(lang.values.loyaltyPointsDisplayFormat, {
          loyaltyPoints: userService.getLoyaltyCard().points_balance,
        }),
    }
  },
]
