/**
 * Turns the value of a context param into a json blob
 * See app/angular/services/modules/userOverridesV2.service.js
 *
 * @param {object} paramsObject  - All params from the url
 * @param {string} name          - The param to select
 * @param {object} ALLOWED_OVERRIDES   - The params to allow, as a map with their types
 *
 * @returns {object}
 */
export default function parse(paramsObject, name, ALLOWED_OVERRIDES) {
  const urlParamValue = paramsObject[name]
  if (!urlParamValue) return null

  // Regexp" Insert quotes around key/values (fix ending for validity)
  const jsonReadyValue = `${urlParamValue}${!urlParamValue.endsWith(',') ? ',' : ''}`
    .replace(/(.+?):(.+?),/g, '"$1":"$2",')
    .slice(0, -1)

  try {
    const overrides = JSON.parse(`{${jsonReadyValue}}`)

    // Filter out overrides not in ALLOWED_OVERRIDES, convert those that are to the correct type
    return Object.entries(overrides).reduce((out, [key, value]) => {
      const newOut = key in ALLOWED_OVERRIDES ? { [key]: ALLOWED_OVERRIDES[key](value) } : {}
      return { ...out, ...newOut }
    }, {})
  } catch (e) {
    console.warn(`Could not process url "${name}" param - wrong format`, urlParamValue, e)
    return null
  }
}
