import _ from 'lodash'
import angular from 'angular'
import dayjs from 'dayjs'
import { PAYMENT_POLYTYPES } from '@app/angular/constants/payments'
import { configurablePayeezy } from './payeezy.js'

export default angular
  .module('FirstDataIntegrationControllerModule', [])
  .controller(
    'FirstDataIntegrationController',
    function (
      $filter,
      $rootScope,
      $scope,
      $timeout,
      Restangular,
      config,
      errorModalService,
      firstDataPayment,
      lang
    ) {
      const checkoutNewCreditCard =
        $scope.checkoutNewCreditCard || $scope.$parent.checkoutNewCreditCard
      const setCreditCard = () => $scope.doSave && $scope.doSave()

      const Payeezy = configurablePayeezy(config)
      window.Payeezy = Payeezy

      $scope.expiryMonth = [
        { name: 'January', value: '01' },
        { name: 'February', value: '02' },
        { name: 'March', value: '03' },
        { name: 'April', value: '04' },
        { name: 'May', value: '05' },
        { name: 'June', value: '06' },
        { name: 'July', value: '07' },
        { name: 'August', value: '08' },
        { name: 'September', value: '09' },
        { name: 'October', value: '10' },
        { name: 'November', value: '11' },
        { name: 'December', value: '12' },
      ]

      $scope.expiryYear = []

      // eslint-disable-next-line no-plusplus
      for (let i = 0; i <= 15; i++) {
        $scope.expiryYear.push({
          name: dayjs().add(i, 'years').format('YYYY'),
          value: dayjs().add(i, 'years').format('YY'),
        })
      }

      $scope.creditCards = [
        { id: 'mastercard', name: 'Master Card', logo: 'mastercard.svg' },
        { id: 'visa', name: 'Visa', logo: 'visa.svg' },
        { id: 'American Express', name: 'American Express', logo: 'american_express.svg' },
        { id: 'discover', name: 'Discover', logo: 'discover.svg' },
      ]

      function resetForm() {
        $scope.showFirstDataForm = false
        $scope.hideAddCardButton = false

        if ($scope.toggleIsShowingAddCard) {
          $scope.toggleIsShowingAddCard(false)
        }

        $scope.error = ''
      }

      function clearCardData() {
        $scope.cardType = null
        $scope.ccNumber = ''
        $scope.cardDescription = ''
        $scope.zipPostalCode = ''
      }

      clearCardData() // initalize card data

      $scope.showAddCardForm = () => {
        $scope.showFirstDataForm = true
        $scope.hideAddCardButton = true

        if ($scope.toggleIsShowingAddCard) {
          $scope.toggleIsShowingAddCard(true)
        }
      }

      $scope.getPaymentInstruments = function (initCall) {
        return Restangular.one('user')
          .one('payment')
          .get({ payment_type: 'auth_capture' })
          .then(response => {
            const instruments = response.auth_capture.payment_instruments
            const firstInstrument = instruments && instruments.length && instruments[0]

            $scope.paymentInstruments = $filter('orderBy')(
              response.auth_capture.payment_instruments,
              'id',
              true
            )

            firstDataPayment.status.loaded = true

            if ($scope.paymentInstruments.length) {
              if (!$scope.selectedCard) {
                // If nothing chosen, select first one by default
                $scope.selectedCard = firstInstrument
              } else {
                // Make sure previously choice still exists
                const existing = _.find($scope.paymentInstruments, {
                  id: $scope.selectedCard.ext_id,
                })
                if (!existing) {
                  // Default for first one if not
                  $scope.selectedCard = $scope.paymentInstruments[0]
                } else {
                  $scope.selectedCard = existing
                }
              }

              if (initCall) {
                $timeout(() => setCreditCard())
              }
            } else {
              // Handle no payment methods existing
              $scope.selectedCard = null
              $scope.showFirstDataForm = true
              $scope.hideAddCardButton = true

              if ($scope.toggleIsShowingAddCard) {
                $scope.toggleIsShowingAddCard(true)
              }
            }
          })
      }

      $scope.selectedCard = $scope.payment?.auth_capture
      $scope.getPaymentInstruments(true) // Fetch initially

      $scope.addCard = function () {
        const responseHandler = function (status, response) {
          // eslint-disable-next-line eqeqeq
          if (status != 201) {
            $scope.$apply(() => {
              const { messages } = (response && response.Error) || {}
              $scope.error =
                (messages && messages[0] && messages[0].description) ||
                lang.values.defaultErrorMessage
            })

            if (checkoutNewCreditCard) {
              checkoutNewCreditCard(false)
            }
          } else {
            const tokenValue = response.token.value
            const cardholderName = response.token.cardholder_name
            const expiryMonth = response.token.exp_date.slice(0, 2)
            const expiryYear = response.token.exp_date.slice(-2)
            const provider = response.token.type
            const last_four_digits = tokenValue.slice(-4)

            // Object to be sent to backend
            $scope.customerToken = {
              expiry_date: `20${expiryYear}-${expiryMonth}-01`,
              provider,
              last_four_digits,
              holder_name: cardholderName,
              postal_code: $scope.zipPostalCode,
              ext_id: tokenValue,
              polytype: PAYMENT_POLYTYPES.CREDIT_CARD,
              name: $scope.cardDescription,
            }

            Restangular.one('user', 'payment')
              .customPOST($scope.customerToken)
              .then(resetForm)
              .then(clearCardData)
              .then(() => $scope.getPaymentInstruments(false))
              .then(() => {
                $scope.selectedCard = $scope.paymentInstruments[0] // Select most recent card added to wallet

                if (checkoutNewCreditCard) {
                  checkoutNewCreditCard(true)
                }

                $timeout(setCreditCard)
              })
              .catch(err => {
                $scope.error = err?.data?.message || err?.message

                if (checkoutNewCreditCard) {
                  checkoutNewCreditCard(false)
                }
              })
          }
        }

        // Get Auth token
        const auth = document.getElementById('auth')

        Payeezy.setApiKey(config.firstData.apiKey)
        Payeezy.setJs_Security_Key(config.firstData.jsSecurityKey)
        Payeezy.setTa_token(config.firstData.taToken)
        Payeezy.setAuth(auth.value)
        Payeezy.createToken(responseHandler)
      }

      $scope.cancelAddCard = function () {
        $scope.showFirstDataForm = false
        $scope.hideAddCardButton = false
        $scope.error = false

        if ($scope.toggleIsShowingAddCard) {
          $scope.toggleIsShowingAddCard(false)
        }

        clearCardData()
      }

      // Delete Credit Card
      $scope.deleteCard = function (card) {
        const errorServiceModalPromise = errorModalService.open(
          `${lang.template(lang.values.deleteCardMessage, {
            cardName: card.name,
            cardNumber: card.last_four_digits,
          })}`,
          lang.values.confirm,
          lang.values.cancel,
          lang.values.deleteCard
        )

        errorServiceModalPromise
          .then(() => Restangular.one('user/payment', card.id).remove())
          .then(() => $scope.getPaymentInstruments(false))
      }

      // Allow user to proceed once a payment instrument has been selected
      $scope.$watch('selectedCard', () => {
        if ($scope.selectedCard) {
          $scope.payment.auth_capture = {
            payment_instrument: $scope.selectedCard,
          }
        }
        firstDataPayment.status.ready = !!$scope.selectedCard
      })

      $scope.$watch('ccNumber', () => ($scope.cardType = Payeezy.identify($scope.ccNumber)))

      $scope.$watchGroup(['cardType', 'ccNumber'], () => {
        const identifiedType = Payeezy.identify($scope.ccNumber)
        $scope.valid =
          !!$scope.cardType &&
          !!$scope.ccNumber &&
          (identifiedType || identifiedType === $scope.cardType)
      })
    }
  )
