import angular from 'angular'
import { addErrorInterceptor } from '@app/utils/request'

export default angular
  .module('errorInspectorServicesModule', [])
  .run(($rootScope, $state, Restangular, analytics, config, errorModalService, lang) => {
    let connectionModal

    const handleError = (status, url, requestId, message) => {
      const whiteListRoutes = ['/placements', '/user_events', '/facets', '/auth/external']

      // Handle any 5XX errors, except 503 which is handled by the maintenance module
      if ((status.toString()[0] === '5' && status !== 503) || status === 405) {
        // Check if the route URL contains any of the whitelisted routes, if so
        // simply return true (essentially don't break the promise chain, so the
        // caller can implement a .catch() if needed.)
        if (whiteListRoutes.some(route => url.includes(route))) {
          analytics.logEvent('unhandled', 'whitelisted backend error', status)
          return true
        }
        analytics.logEvent('unhandled', 'backend error', status)

        const meta = {}

        if (config.enableErrorRequestID || !['prod', 'production'].includes(process.env.NODE_ENV)) {
          meta.requestId = requestId
        }

        errorModalService
          .open(
            message || config.lang.defaultErrorMessage,
            lang.values.reload,
            lang.values.backToHome,
            undefined,
            undefined,
            meta
          )
          .then(
            () => {
              window.location.reload()
            },
            () => {
              $state.go('main.home')
            }
          )
      }

      // Handle no response from the server

      if (status === 0 && !connectionModal) {
        analytics.logEvent('unhandled', 'backend error', status)
        connectionModal = errorModalService.open(
          'Connection lost...',
          'Retry',
          null,
          'Connection Lost',
          true
        )

        connectionModal.result.then(() => {
          connectionModal = null
          $rootScope.reloadState()
        })
      }
    }

    addErrorInterceptor(response => {
      response
        .json()
        .then(res => {
          const requestId = response.headers.get('x-event-id')
          handleError(res.status, response.url, requestId, res.message)
        })
        .catch(() => {
          handleError(response.status, response.url)
        })
    })

    Restangular.setErrorInterceptor(response => {
      const { status, config, headers, message } = response
      handleError(status, config.url, headers()['x-event-id'], message)
    })
  })
