import angular from 'angular'
import template from './couponClipper.html'

export default angular.module('CouponClipperModule', []).directive('couponClipper', function () {
  return {
    scope: {
      coupon: '=',
      couponclass: '=',
      location: '@',
      isDisabled: '<?',
    },
    replace: true,
    template,
    controller(
      $rootScope,
      $scope,
      $element,
      $attrs,
      analytics,
      cartService,
      config,
      lang,
      localStorageService,
      offerAnalytics,
      offersService,
      offerDetailsModalService,
      rateApp
    ) {
      $scope.config = config
      $scope.lang = lang
      const couponTrack = increment => {
        const rateClip = config.mobileAppRate.couponsClipped
        // eslint-disable-next-line eqeqeq
        if (rateClip != -1) {
          const coupons_clipped = localStorageService.deprecatedGet('coupons_clipped')
          if (coupons_clipped + increment >= rateClip) {
            // re-set clipped coupons
            localStorageService.deprecatedSet('coupons_clipped', 0)
            // prompt review
            rateApp.promptAppReview()
          } else {
            // update coupons clipped
            localStorageService.deprecatedSet('coupons_clipped', coupons_clipped + increment)
          }
        }
      }

      $scope.toggleClippedState = function () {
        if (!$scope.coupon.clipped || !config.disableCouponUnclipping) {
          $scope.coupon.clipped = !$scope.coupon.clipped
          offersService.updateClippedState($scope.coupon)
          // Log analytics when coupon has been clipped
          if ($scope.coupon.clipped) {
            if (!$rootScope.modifyingOrder) {
              analytics.logOfferClick($scope.coupon, $scope.location)
              offerAnalytics.addOffer({ offer: $scope.coupon })
            }

            if (config.openOfferAfterCouponClip) {
              offerDetailsModalService.open($scope.coupon)
            }

            // check app review if clipped
            couponTrack(1)
          }
        }
      }
      $scope.isCartSyncing = () => cartService.syncing
    },
  }
})
