import React, { Fragment } from 'react'
import { Button, Text, createStyles } from '@instacart/cocktail'
import { lang as langService } from '@instacart/enterprise-services'
import dayjs from 'dayjs'
import advancedFormat from 'dayjs/plugin/advancedFormat'
import PropTypes from 'prop-types'
import { SUBSCRIPTION_PATHS } from '../constants'
import Header from '../Header'

dayjs.extend(advancedFormat)

const useStyles = createStyles(tokens => ({
  root: {
    marginBottom: tokens.size.layout.xLarge,
    paddingLeft: tokens.size.spacing.medium,
    paddingRight: tokens.size.spacing.medium,
  },
  buttonContainer: {
    display: 'flex',
    justifyContent: 'center',
    margin: tokens.size.spacing.medium,
  },
  secondMessage: {
    marginTop: tokens.size.layout.medium,
  },
  buyingMessage: {
    a: {
      color: tokens.color.brand.primary.dark,
    },
  },
  header: {
    padding: tokens.size.spacing.medium,
    marginBottom: tokens.size.spacing.xxLarge,
  },
}))

const defaultLang = {
  continueButton: 'Start Shopping',
  buying: {
    message1: `Thank you for subscribing to Fresh Grocery. Like thousands of other members, you can now enjoy the convenience of
       FREE pick fees and exclusive promotions for Fresh Grocery shoppers for a whole year.`,
    message2:
      'For further information about your subscription, please visit your <a>Profile</a> page on freshgrocery.com.',
  },
  gifting: {
    message1:
      'Gift subscriptions were purchased for: <b>{{email}}</b> and will be delivered on <b>{{date}}</b>.',
    message2: `A redemption code and further instructions will be sent to the recipient through email. Customers will
       have 7 days to redeem the subscription. If unredeemed for 7 days, you will recieve a refund for the subscription amount.`,
  },
}

const SubscriptionSuccess = props => {
  const styles = useStyles(props)

  const { path, i18n, email, date, onContinueClicked } = props
  const lang = { ...defaultLang, ...i18n }

  const htmlVars = {
    email: email || '',
    date: (date && dayjs(date).format('MMMM Do, YYYY')) || '',
  }

  return (
    <Fragment>
      <Header css={styles.header} i18n={i18n.header[path] || i18n.header.default} />
      <div css={styles.root}>
        <Text
          variant="large"
          color="textPrimary"
          dangerouslySetInnerHTML={{
            __html: langService.interpolate(lang[path].message1, htmlVars),
          }}
        />
        {path === SUBSCRIPTION_PATHS.GIFTING && (
          <Text css={styles.secondMessage} variant="large" color="textPrimary">
            {lang[path].message2}
          </Text>
        )}
        <div css={styles.buttonContainer}>
          <Button onClick={onContinueClicked}>{lang.continueButton}</Button>
        </div>
        {path === SUBSCRIPTION_PATHS.BUYING && (
          <Text
            css={styles.buyingMessage}
            variant="large"
            color="textPrimary"
            dangerouslySetInnerHTML={{ __html: lang[path].message2 }}
          />
        )}
      </div>
    </Fragment>
  )
}

SubscriptionSuccess.propTypes = {
  /** Path: Determine if we're gifting or buying */
  path: PropTypes.string.isRequired,
  /** Language object for overrides */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** email to send to if gifting (display only) */
  email: PropTypes.string,
  /** Date to send subscription if gifting (display only) */
  // eslint-disable-next-line react/forbid-prop-types
  date: PropTypes.object,
  /** function to call when the continue button is clicked */
  onContinueClicked: PropTypes.func,
}

export default SubscriptionSuccess
