import angular from 'angular'
import SnapEBTAddModalController from './snapEBTAddModalController'
import SnapEBTRemoveModalController from './snapEbtRemoveModalController'

export default angular
  .module('SnapEBTComponentModule', [
    SnapEBTAddModalController.name,
    SnapEBTRemoveModalController.name,
  ])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.account.snapEBT', {
      url: '/snap-ebt',
      views: {
        'content@main.account': {
          template: require('./views/snapEBT.html'),
          controller: 'SnapEBTController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "snapEBTController", webpackPrefetch: true */ './snapEBTController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.ebt.EBTSNAP)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.ebt.EBTSNAP,
          },
        ],
      },
    })
  })
