/* eslint-disable max-lines */

import angular from 'angular'
import isMobile from '@app/utils/isMobile'
import isMobileApp from '@app/utils/isMobileApp'
import { ANALYTICS_VALUES } from '@app/angular/constants/analyticsValues'
import { COUPON_TAGS } from '@app/angular/constants/tags'
import { COUPON_TYPES, COUPON_SOURCES } from '@app/angular/constants/coupons'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { SCRIPT_TIMEOUT } from '@app/angular/constants/personalizationLoading'
import appendMetaTagDescription from '@app/utils/appendMetaTagDescription'
import { deleteJsonLd } from '@app/utils/jsonLd'
import { isWaysToShopEnabled } from '@app/utils/isModuleEnabled'
import {
  getAll as getMerchandisedLandingPages,
  getOne as getMerchandisedLandingPage,
} from '@app/services/merchandisedLandingPages/api'
import { inCateringContext } from '@app/services/shoppingContext/selectors'

let _offerAnalytics = null
let _removeAuthListener = null

export default angular
  .module('ShopComponentModule', [])
  .config(($stateProvider, configConstants) => {
    // Reused for multiple views
    // Must be duplicated to force a refresh of the
    // menu when state changes

    const shopViews = {
      'content@': {
        template: require('./views/shop.html'),
      },
      'content@main.shop': {
        template: require('./views/waysToShop.html'),
        controller: 'ShopHomeController',
      },
    }

    const shopContentViews = {
      'content@main.shop': {
        template: require('./views/shopContent.html'),
        controller: 'ShopContentController',
      },
    }

    const shopCollectionViews = {
      'content@main.shop': {
        template: require('./views/collectionsHome.html'),
        controller: 'CollectionsHomeController',
      },
    }

    const shopDepartmentViews = {
      'content@main.shop': {
        template: require('./views/featuredDepartment.html'),
        controller: 'FeaturedDepartmentController',
        controllerAs: '$ctrl',
      },
    }

    const shopMerchandisedLandingPageViews = {
      'content@main.shop': {
        template: require('./views/merchandisedLandingPage.html'),
        controller: 'MerchandisedLandingPageController',
        controllerAs: '$ctrl',
      },
    }

    const shopFlyerFlippViews = {
      'content@main.shop': {
        controller: 'FlyerFlippController',
        template: require('./views/flyerFlipp.html'),
        controllerAs: '$ctrl',
      },
    }

    shopFlyerFlippViews['desktopMenu@main.shop'] = {
      template: require('./views/empty.html'),
    }

    // Use mobile shop menu on mobile devices and Cordova
    if (isMobile(configConstants) || isMobileApp()) {
      shopViews['mobileMenu@main.shop'] = {
        template: require('./views/shopMenu.mobile.html'),
        controller: 'ShopMenuController',
      }

      shopContentViews['mobileMenu@main.shop.*'] = {
        template: require('./views/shopMenu.mobile.html'),
        controller: 'ShopMenuController',
      }

      shopCollectionViews['mobileMenu@main.shop.collections'] = {
        template: require('./views/shopMenu.mobile.html'),
        controller: 'ShopMenuController',
      }

      shopDepartmentViews['mobileMenu@main.shop.departments'] = {
        template: require('./views/shopMenu.mobile.html'),
        controller: 'ShopMenuController',
      }

      shopMerchandisedLandingPageViews['mobileMenu@main.shop.mlp'] = {
        template: require('./views/shopMenu.mobile.html'),
        controller: 'ShopMenuController',
      }
    } else {
      shopViews['desktopMenu@main.shop'] = {
        template: require('./views/shopMenu.desktop.html'),
        controller: 'ShopMenuController',
      }

      shopContentViews['desktopMenu@main.shop'] = {
        template: require('./views/shopMenu.desktop.html'),
        controller: 'ShopMenuController',
      }

      shopCollectionViews['desktopMenu@main.shop'] = {
        template: require('./views/shopMenu.desktop.html'),
        controller: 'ShopMenuController',
      }

      shopDepartmentViews['desktopMenu@main.shop'] = {
        template: require('./views/shopMenu.desktop.html'),
        controller: 'ShopMenuController',
      }

      shopMerchandisedLandingPageViews['desktopMenu@main.shop'] = {
        template: require('./views/shopMenu.desktop.html'),
        controller: 'ShopMenuController',
      }
    }

    function loadShopContentViewsCtrl($ocLazyLoad) {
      const importShopMenuController = import(
        /* webpackChunkName: "shopMenuController", webpackPrefetch: true */ './controllers/shopMenuController'
      )
      const importShopContentController = import(
        /* webpackChunkName: "shopContentController", webpackPrefetch: true */ './controllers/shopContentController'
      )

      return Promise.all([importShopMenuController, importShopContentController])
        .then(values => $ocLazyLoad.load(values.map(module => module.default)))
        .catch(err => {
          throw new Error(`Ooops, something went wrong, ${err}`)
        })
    }

    const recipesTreeStates = {
      recipesHome: {
        name: configConstants.lang.recipes,
        state: 'main.shop.recipes',
      },
      savedRecipes: {
        name: configConstants.lang.savedRecipes.savedRecipes,
        state: 'main.shop.recipes.saved',
      },
    }

    // Decorator to handle DRY views objects
    // Replaces any state declaration that uses a '*' with the exact current state

    $stateProvider.decorator('views', (state, parent) => {
      const result = {}
      const views = parent(state)

      angular.forEach(views, (configConstants, name) => {
        if (name.indexOf('*') > 0) {
          name = name.substring(0, name.indexOf('@') + 1) + state.name
        }
        result[name] = configConstants
      })

      return result
    })

    // Make things more DRY, we use these quite a bit to refer to parents, etc..

    const shopContentParents = {
      collections: {
        name: configConstants.lang.collections,
        state: 'main.shop.collections',
      },

      coupons: {
        name: configConstants.lang.coupons,
        state: 'main.shop.coupons',
      },

      offers: {
        name: configConstants.lang.offers,
        state: 'main.shop.offers',
      },

      waysToShop: {
        name: configConstants.lang.waysToShop,
        state: 'main.shop',
        params: { tags: null, brand_name: null },
      },

      weeklyAd: {
        name: configConstants.lang.weeklyAd,
        state: 'main.shop.flyer',
        link: configConstants.dotComModule.weeklyAdPath
          ? configConstants.dotComModule.url + configConstants.dotComModule.weeklyAdPath
          : '',
      },

      offersAndCoupons: {
        name: configConstants.lang.offersAndCoupons,
        state: 'main.shop.offersAndCoupons',
      },

      departments: {
        name: configConstants.lang.departments,
        state: 'main.shop.departments',
      },
    }

    // Initialize states

    $stateProvider

      // Main shop route + shop homepage

      .state('main.shop', {
        url: '/shop?tags&sort&page&brand_name&brand_names&offer_sources&group_display_name',

        data: {
          section: 'catalog',
        },

        resolve: {
          loadCtrl: $ocLazyLoad => {
            const importShopMenuController = import(
              /* webpackChunkName: "shopMenuController", webpackPrefetch: true */ './controllers/shopMenuController'
            )
            const importShopHomeController = import(
              /* webpackChunkName: "shopHomeController", webpackPrefetch: true */ './controllers/shopHomeController'
            )

            return Promise.all([importShopMenuController, importShopHomeController])
              .then(values => $ocLazyLoad.load(values.map(module => module.default)))
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },

          tree(lang) {
            const tree = {
              current: shopContentParents.waysToShop,
              children: [],
            }

            tree.children.push({
              name: lang.values.departments,
              state: 'main.shop.categories',
            })

            const purchasedExists = configConstants.tags.find(tag => tag.id === 'purchased')
            if (purchasedExists) {
              tree.children.push({
                name: configConstants.lang.previousPurchases,
                state: 'main.shop.purchases',
              })
            }

            if (!configConstants.disableWeeklyAd) {
              tree.children.push(shopContentParents.weeklyAd)
            }

            if (!configConstants.disableCollections) {
              tree.children.push(shopContentParents.collections)
            }

            if (!configConstants.disableCoupons) {
              tree.children.push(shopContentParents.coupons)
            }
            if (configConstants.waysToShopLinks && configConstants.waysToShopLinks.length) {
              configConstants.waysToShopLinks.forEach(link => {
                tree.children.push({ ...link })
              })
            }

            return tree
          },
        },

        views: shopViews,

        onEnter($rootScope, $state, $stateParams, tagsService) {
          // If any of the tags in the new state's params require registration, ensure the user either registers or is logged in
          if ($stateParams.tags && $stateParams.tags.length > 0) {
            const activeTags = Array.isArray($stateParams.tags)
              ? angular.copy($stateParams.tags)
              : [$stateParams.tags]

            return tagsService.checkRegistrationRequired({
              activeTags,
              allTags: tagsService.tags(),
              success: () => {}, // User gets to continue with the route change
              failure: () => {
                // Did not login/register, refresh the page without the required filter/tags (removed in checkRegistrationRequired)
                return $state.go('.', { tags: activeTags, page: null }, { reload: true })
              },
            })
          }
        },
      })

      // Shop categories front page

      .state('main.shop.categories', {
        url: '/categories',

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams, configConstants) {
            // Checks if client has a custom `offer_sources` for `on_sale` products
            if (
              $stateParams.tags &&
              $stateParams.tags.indexOf('on_sale') > -1 &&
              configConstants.onSaleOfferSources
            ) {
              $stateParams.offer_sources = configConstants.onSaleOfferSources
            }

            // Checks if client has a custom `offer_sources` for `has_coupon` products
            if (
              $stateParams.tags &&
              $stateParams.tags.indexOf('has_coupon') > -1 &&
              configConstants.hasCouponOfferSources
            ) {
              $stateParams.offer_sources = configConstants.hasCouponOfferSources
            }

            // Checks if client has a custom `offer_sources` for `has_offer` products
            if (
              $stateParams.tags &&
              $stateParams.tags.indexOf('has_offer') > -1 &&
              configConstants.hasOfferOfferSources
            ) {
              $stateParams.offer_sources = configConstants.hasOfferOfferSources
            }

            // Show subcategories unless tags or brands are applied,
            // otherwise show products

            return {
              endpoint:
                ($stateParams.tags && $stateParams.tags.length) || $stateParams.brand_name?.length
                  ? 'store_products'
                  : null,
            }
          },

          categories(catalogCategoriesService, eComLightService) {
            return catalogCategoriesService
              .fetchCategories()
              .then(categories => categories.filter(eComLightService.isNonEComLightCategory))
          },

          async merchandisedLandingPageCategories($location, configConstants) {
            // this applies to desktop only because MLPs are persisted through the fixed header nav, to prevent fetching on every child category route
            // in mobile, we should be fetching MLPs on this route every time because there is no persistent nav
            if (!$location.path().endsWith('/categories') && !isMobile(configConstants)) {
              return []
            }
            const merchandisedLandingPages = await getMerchandisedLandingPages()

            return merchandisedLandingPages.items.map(mlp => {
              const imageUrl =
                isMobile(configConstants) || isMobileApp()
                  ? mlp.images?.mobile?.url
                  : mlp.images?.desktop?.url
              return {
                ...mlp,
                type: 'mlp_v2',
                media: {
                  image: imageUrl,
                },
              }
            })
          },

          tree(
            $q,
            lang,
            categories,
            catalogCategoriesService,
            merchandisedLandingPageCategories,
            tagsService
          ) {
            return {
              current: {
                name: lang.values.departments,
                state: 'main.shop.categories',
                sort: true,
                tags: tagsService.filterBarTags,
                level: -1,
              },
              parents: !isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop],
              children: catalogCategoriesService.visibleRootCategories(
                merchandisedLandingPageCategories
              ),
              siblings: [],
            }
          },
        },

        views: shopContentViews,

        params: {
          toggledFilterId: null,
        },
      })

      // Shop category

      .state('main.shop.category', {
        parent: 'main.shop.categories',
        url: '/:category_id',

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,

          category($stateParams, catalogCategoriesService) {
            const category = catalogCategoriesService.categoryFromId($stateParams.category_id)

            // If the category doesn't exist in memory, we try once more after fetching a new list of categories
            if (!category) {
              return catalogCategoriesService
                .fetchCategories(true)
                .then(() => catalogCategoriesService.categoryFromId($stateParams.category_id))
            }

            return category
          },

          src($state, $stateParams, category) {
            if (!category) {
              $state.go('main.404')
            }
            // Checks if client has a custom `offer_sources` for `has_coupon` products
            if (
              $stateParams.tags &&
              $stateParams.tags.indexOf('has_coupon') > -1 &&
              configConstants.hasCouponOfferSources
            ) {
              $stateParams.offer_sources = configConstants.hasCouponOfferSources
            }

            // Checks if client has a custom `offer_sources` for `has_offer` products
            if (
              $stateParams.tags &&
              $stateParams.tags.indexOf('has_offer') > -1 &&
              configConstants.hasOfferOfferSources
            ) {
              $stateParams.offer_sources = configConstants.hasOfferOfferSources
            }

            // Show product tiles instead of product category tiles starting on this productCategory level (and deeper)
            const categoryLevelToShowProductTiles = configConstants.categoryLeveltoShowProducts

            return {
              endpoint:
                category.level >= categoryLevelToShowProductTiles ||
                $stateParams.tags?.length ||
                $stateParams.brand_name ||
                !category.children?.length
                  ? 'store_products'
                  : null,
            }
          },

          catering: (
            $rootScope,
            $stateParams,
            auth,
            category,
            config,
            eComLightService,
            localStorageService
          ) => {
            const isEComLightEnabled =
              config.eComLight?.enabled && config.isDedicatedCateringExperienceSupported
            const isInEComLightMode = eComLightService.inEComLightMode
            const ecomLightCategoryID = config.eComLight?.homeRedirect?.params?.category_id
            const hasEComLightCategory =
              ecomLightCategoryID &&
              ($stateParams.category_id === ecomLightCategoryID ||
                category.parents.find(category => category.id === ecomLightCategoryID))

            /**
             * If the retailer has ecomLight enabled and the user is accessing a subcategory of
             * ecomlight, set ecomlight mode to true
             */
            if (isEComLightEnabled && !isInEComLightMode && hasEComLightCategory) {
              eComLightService.setMode(true)
              // This is needed as this intent change will break the shopping selector popping up
              localStorageService.deprecatedSet(
                'userCartIntent',
                auth.getCurrentShoppingMode() || auth.user.context.intent
              )
              return auth.setIntent(SHOPPING_MODES.CATERING)
            }
          },

          onEnter: (lang, tree) => {
            appendMetaTagDescription(
              lang.templateAsString(lang.values.metaTagCategoryDefault, {
                category: tree?.current?.name?.toLowerCase() || '',
                retailer: lang.values.clientName,
              })
            )
          },

          tree: (
            $q,
            $stateParams,
            category,
            catalogCategoriesService,
            config,
            eComLightService,
            lang
          ) => {
            let { parents } = category

            if (!eComLightService.inEComLightMode) {
              const facetsEnabled =
                configConstants.enableFacetedSearchFilters || configConstants.enableICSearch

              // Only show the Ways To Shop and Departments links if not in eCom Light mode
              parents = [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                {
                  name: lang.values.departments,
                  state: 'main.shop.categories',
                  params: { ...(!facetsEnabled && { brand_name: null }) },
                },
                ...parents,
              ]
            }

            const siblings =
              eComLightService.inEComLightMode && category.id === eComLightService.eComLightCategory
                ? []
                : catalogCategoriesService.siblings(category)

            return {
              current: category,
              parents,
              children: catalogCategoriesService.children(category),
              siblings,
            }
          },
        },

        views: shopContentViews,
      })

      // All previous purchases

      .state('main.shop.purchases', {
        url: '/purchases',

        data: {
          section: 'purchases',
          requireRegistered: true,
        },

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src() {
            return {
              endpoint: 'store_products',
              params: {
                tags: ['purchased'],
              },
            }
          },

          purchasesCategories(purchasesCategoriesService) {
            return purchasesCategoriesService.fetch()
          },

          tree(purchasesCategories, tagsService) {
            return {
              current: {
                name: configConstants.lang.previousPurchases,
                state: 'main.shop.purchases',
                sort: true,
                tags: tagsService.tags({ id: 'purchased' }), // Use all tags except purchased (as it's redundant here)
              },
              children: configConstants.shopCollections.purchases.concat(purchasesCategories),
              parents: !isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop],
            }
          },
        },

        views: shopContentViews,
      })

      // Previous purchases collection

      .state('main.shop.purchases.collection', {
        url: '/collection/:collection_id',

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams) {
            return { endpoint: `collections/${$stateParams.collection_id}` }
          },

          tree($stateParams, purchasesCategories, tagsService) {
            const collection = configConstants.shopCollections.purchases.find(
              collection =>
                collection.params.collection_id.toString() === $stateParams.collection_id
            )

            return {
              current: {
                name: collection.name,
                state: 'main.shop.purchases.collection',
                sort: true,
                tags: tagsService.tags({ id: 'purchased' }), // Use all tags except purchased (as it's redundant here)
              },
              children: [],
              siblings: configConstants.shopCollections.purchases.concat(purchasesCategories),
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                {
                  name: configConstants.lang.previousPurchases,
                  state: 'main.shop.purchases',
                  sort: true,
                  tags: tagsService.tags({ id: 'purchased' }), // Use all tags except purchased (as it's redundant here)
                },
              ],
            }
          },
        },

        views: shopContentViews,
      })

      // Individual previous purchases

      .state('main.shop.purchases.purchase', {
        url: '/purchase/:purchase_id',

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams) {
            return {
              endpoint: 'store_products',
              params: {
                purchase_id: $stateParams.purchase_id,
              },
            }
          },

          tree($stateParams, purchasesCategories, tagsService) {
            const purchase = purchasesCategories.find(
              purchase => purchase.params.purchase_id.toString() === $stateParams.purchase_id
            )

            return {
              current: purchase,
              children: [],
              siblings: configConstants.shopCollections.purchases.concat(purchasesCategories),
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                {
                  name: configConstants.lang.previousPurchases,
                  state: 'main.shop.purchases',
                  sort: true,
                  tags: tagsService.tags({ id: 'purchased' }), // Use all tags except purchased (as it's redundant here)
                },
              ],
            }
          },
        },

        views: shopContentViews,
      })

      // Flyer front page

      .state('main.shop.flyer', {
        url: '/flyer?preview_timestamp&show_first_preview',

        data: {
          section: 'flyerhomepage',
        },

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          // Set previewTimestamp or previewToken in flyerPreviewParams to be used by future resolves
          flyerPreviewParams($stateParams, auth, flyerPreviewService) {
            const previewTimestamp = $stateParams.preview_timestamp

            if ($stateParams.preview_timestamp) {
              // If there's a preview_timestamp in the address bar, set it in the flyerPreviewService and show that flyer.
              flyerPreviewService.setPreviewTimestamp(previewTimestamp)
              return { preview_timestamp: previewTimestamp }
            }
            // Otherwise, fetch flyer preview data before page load - data will be stored and accessible via flyerPreviewService.
            // (We only do this if preview_timestamp is not provided, otherwise they'd clash awkwardly.
            //  We do this for backwards compatibility for Dashboard Flyer preview - preview_timestamp always takes priority.)
            return flyerPreviewService.fetchFlyerData(auth.getStore()).then(() => {
              if ($stateParams.show_first_preview) {
                // If there's the param "show_first_preview" in the address bar, set the first available preview token in the flyerPreviewService.
                flyerPreviewService.setFirstAvailablePreviewToken()
              }
              // Show whatever preview flyer whose preview token is set in flyerPreviewService
              //  (if no preview token is set it'll return null and we just show the regular flyer page).
              return { preview_token: flyerPreviewService.getPreviewToken() }
            })
          },

          src(flyerPreviewParams) {
            return {
              endpoint: 'flyer',
              params: flyerPreviewParams,
            }
          },

          flyerCategories(flyerCategoriesAndPdfsService, flyerPreviewParams) {
            return flyerCategoriesAndPdfsService.fetchFlyerCategories(flyerPreviewParams)
          },

          flyerPdfHrefs(flyerCategoriesAndPdfsService, flyerPreviewParams, flyerPreviewService) {
            // Make sure we've resolved `flyerPreviewParams` to ensure the flyer preview data has been fetched
            return flyerCategoriesAndPdfsService.fetchFlyerPdfHrefs(
              flyerPreviewService.getFlyerData(),
              flyerPreviewService.getPreviewTimestamp()
            )
          },

          // PDF href for current flyer being viewed (regular or preview)
          currentFlyerPdfHref(flyerPdfHrefs, flyerPreviewParams, flyerPreviewService) {
            const isPreview = flyerPreviewParams && flyerPreviewParams.preview_token

            let flyerPdfHref = ''
            if (isPreview) {
              const flyerPreviewIndex = flyerPreviewService.getPreviewFlyerIndexBeingViewed()
              if (flyerPreviewIndex > -1) {
                flyerPdfHref = flyerPdfHrefs.previews[flyerPreviewIndex]
              }
            } else {
              flyerPdfHref = flyerPdfHrefs.current
            }
            return flyerPdfHref
          },

          tree(currentFlyerPdfHref, eComLightService, flyerCategories, flyerPreviewParams) {
            const isPreview = flyerPreviewParams && flyerPreviewParams.preview_token
            let weeklyAdChildLinks = isPreview
              ? configConstants.shopCollections.flyer.filter(links => !links.hideWhenPreviewing)
              : configConstants.shopCollections.flyer
            weeklyAdChildLinks = eComLightService.inEComLightMode
              ? configConstants.shopCollections.flyer.filter(links => !links.hideWhenInEcomLight)
              : weeklyAdChildLinks
            weeklyAdChildLinks = weeklyAdChildLinks.concat(flyerCategories)

            const currentLinkParams = {}
            if (flyerPreviewParams.preview_timestamp) {
              currentLinkParams.preview_timestamp = flyerPreviewParams.preview_timestamp
            }

            return {
              current: {
                name: isPreview
                  ? `${configConstants.lang.weeklyAd} Preview`
                  : configConstants.lang.weeklyAd,
                state: 'main.shop.flyer',
                params: currentLinkParams,
                sort: false,
                tags: null,
                pageHeaderLink: {
                  label: configConstants.lang.downloadPdf,
                  href: configConstants.dotComModule.weeklyAdPath
                    ? configConstants.dotComModule.url + configConstants.dotComModule.weeklyAdPath
                    : currentFlyerPdfHref,
                  target: configConstants.dotComModule.weeklyAdPath ? undefined : '_blank',
                  eventName: ANALYTICS_VALUES.GA_FLYER_CLICK,
                },
              },
              parents: !isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop],
              children: weeklyAdChildLinks,
              siblings: [],
            }
          },
        },

        views: shopContentViews,
      })

      // Flyer collection

      .state('main.shop.flyer.collection', {
        url: '/collection/:collection_id',

        data: {
          section: 'flyer',
        },

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams, flyerPreviewParams) {
            return {
              endpoint: `collections/${$stateParams.collection_id}`,
              params: flyerPreviewParams,
            }
          },

          tree(
            $stateParams,
            currentFlyerPdfHref,
            eComLightService,
            flyerCategories,
            flyerPreviewParams
          ) {
            const collection = configConstants.shopCollections.flyer.find(
              collection =>
                collection.params.collection_id.toString() === $stateParams.collection_id
            )

            const isPreview = flyerPreviewParams && flyerPreviewParams.preview_token
            let weeklyAdChildLinks = isPreview
              ? configConstants.shopCollections.flyer.filter(links => !links.hideWhenPreviewing)
              : configConstants.shopCollections.flyer
            weeklyAdChildLinks = eComLightService.inEComLightMode
              ? configConstants.shopCollections.flyer.filter(links => !links.hideWhenInEcomLight)
              : weeklyAdChildLinks
            weeklyAdChildLinks = weeklyAdChildLinks.concat(flyerCategories)

            const weeklyAdParentName = isPreview
              ? `${configConstants.lang.weeklyAd} Preview`
              : configConstants.lang.weeklyAd

            const currentLinkParams = { collection_id: $stateParams.collection_id }
            if (flyerPreviewParams.preview_timestamp) {
              currentLinkParams.preview_timestamp = flyerPreviewParams.preview_timestamp
            }
            const treeToReturn = {
              current: {
                name: collection.name,
                state: 'main.shop.flyer.collection',
                params: currentLinkParams,
              },
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                { ...shopContentParents.weeklyAd, name: weeklyAdParentName },
              ],
              children: [],
              siblings: weeklyAdChildLinks,
            }

            if (
              configConstants.showFlyerPdfInSpecialsFlyerCollection &&
              $stateParams.collection_id === 'specials'
            ) {
              treeToReturn.current.pageHeaderLink = {
                label: configConstants.lang.downloadPdf,
                href: configConstants.dotComModule.weeklyAdPath
                  ? configConstants.dotComModule.url + configConstants.dotComModule.weeklyAdPath
                  : currentFlyerPdfHref,
                target: configConstants.dotComModule.weeklyAdPath ? undefined : '_blank',
                eventName: ANALYTICS_VALUES.GA_FLYER_CLICK,
              }
            }

            return treeToReturn
          },
        },

        views: shopContentViews,
      })

      // Flyer category

      .state('main.shop.flyer.category', {
        url: '/category/:category_id',

        data: {
          section: 'flyer',
        },

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams, flyerPreviewParams) {
            return {
              endpoint: `flyer/categories/${$stateParams.category_id}`,
              params: flyerPreviewParams,
            }
          },

          tree($stateParams, eComLightService, flyerCategories, flyerPreviewParams, tagsService) {
            const category = flyerCategories.find(
              category => category.id.toString() === $stateParams.category_id
            )

            const isPreview = flyerPreviewParams && flyerPreviewParams.preview_token
            let weeklyAdChildLinks = isPreview
              ? configConstants.shopCollections.flyer.filter(links => !links.hideWhenPreviewing)
              : configConstants.shopCollections.flyer
            weeklyAdChildLinks = eComLightService.inEComLightMode
              ? configConstants.shopCollections.flyer.filter(links => !links.hideWhenInEcomLight)
              : weeklyAdChildLinks
            weeklyAdChildLinks = weeklyAdChildLinks.concat(flyerCategories)

            const weeklyAdParentName = isPreview
              ? `${configConstants.lang.weeklyAd} Preview`
              : configConstants.lang.weeklyAd

            return {
              current: {
                ...category,
                tags: tagsService.filterBarTags,
              },
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                { ...shopContentParents.weeklyAd, name: weeklyAdParentName },
              ],
              children: [],
              siblings: weeklyAdChildLinks,
            }
          },
        },

        views: shopContentViews,
      })

      // Coupons front page

      .state('main.shop.coupons', {
        url: '/coupons',

        data: {
          section: 'coupons',
        },

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams) {
            const srcResult = {
              endpoint: '/offers',
              callback: payload => _offerAnalytics.viewOffers(payload),
            }

            const instacartOnlineOnly = COUPON_TAGS.INSTACART_ONLINE_ONLY
            const couponParams = { type: COUPON_TYPES.COUPON }

            // if the instacart online only fitler was selected, add a
            // source of COUPON_SOURCES.INSTACART
            if (
              (Array.isArray($stateParams.tags) &&
                $stateParams.tags.includes(instacartOnlineOnly)) ||
              $stateParams.tags === instacartOnlineOnly
            ) {
              Object.assign(couponParams, { source: COUPON_SOURCES.INSTACART })
            }

            srcResult.params = configConstants.offersAndCoupons.showCouponsTogether
              ? couponParams
              : Object.assign(couponParams, configConstants.offersAndCoupons.queries.coupons)

            return srcResult
          },

          couponsCategories(couponsCategoriesService) {
            return couponsCategoriesService.fetch()
          },

          tree(couponsCategories, tagsService) {
            return {
              current: {
                ...shopContentParents.coupons,
                tags: tagsService.couponTags,
              },
              children: couponsCategories,
              parents: !isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop],
            }
          },
        },

        views: shopContentViews,

        onEnter($rootScope, $state) {
          if (!_removeAuthListener) {
            _removeAuthListener = $rootScope.$on('auth', (event, action) => {
              if (action === 'userLoggedIn') {
                $state.reload()
              }
            })
          }
        },

        onExit() {
          if (_removeAuthListener) {
            _removeAuthListener() // Call here removes the auth listener
            _removeAuthListener = null
          }
        },
      })

      // Coupons category

      .state('main.shop.coupons.category', {
        url: '/category/:category_id',

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams) {
            return {
              endpoint: '/offers',
              callback: payload => _offerAnalytics.viewOffers(payload),
              params: {
                type: 'coupon',
                category_id: $stateParams.category_id,
              },
            }
          },

          tree($stateParams, couponsCategories) {
            const category = couponsCategories.find(
              category => category.id.toString() === $stateParams.category_id
            )

            return {
              current: category,
              siblings: couponsCategories,
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                shopContentParents.coupons,
              ],
            }
          },
        },

        views: shopContentViews,
      })

      // Offers front page

      .state('main.shop.offers', {
        url: '/offers',

        data: {
          section: 'offers',
        },

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src() {
            return {
              endpoint: '/offers',
              callback: payload => _offerAnalytics.viewOffers(payload),
              params: {
                type: 'offer',
                ...configConstants.offersAndCoupons.queries.offers,
              },
            }
          },

          tree(tagsService) {
            return {
              current: {
                ...shopContentParents.offers,
                tags: tagsService.offerTags,
              },
              children: [],
              parents: !isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop],
            }
          },
        },

        views: shopContentViews,
      })

      // Collections home

      .state('main.shop.collections', {
        url: '/collections',
        data: {
          section: 'collection',
        },

        resolve: {
          loadCtrl: $ocLazyLoad => {
            const importShopMenuController = import(
              /* webpackChunkName: "shopMenuController", webpackPrefetch: true */ './controllers/shopMenuController'
            )
            const importCollectionsHomeController = import(
              /* webpackChunkName: "collectionsHomeController", webpackPrefetch: true */ './controllers/collectionsHomeController'
            )

            return Promise.all([importShopMenuController, importCollectionsHomeController])
              .then(values => $ocLazyLoad.load(values.map(module => module.default)))
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },
          collectionsCategories($stateParams, collectionsCategoriesService) {
            return collectionsCategoriesService.fetch()
          },

          tree(collectionsCategories) {
            return {
              current: shopContentParents.collections,
              parents: !isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop],
              children: collectionsCategories,
            }
          },
          collectionsContent(Restangular) {
            return Restangular.all('placements/shop_collections').getList()
          },
        },

        views: shopCollectionViews,
      })

      // Individual collection

      .state('main.shop.collection', {
        parent: 'main.shop.collections',

        url: '/:collection_id?source',

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams) {
            return { endpoint: `collections/${$stateParams.collection_id}` }
          },

          collectionCategories($stateParams, Restangular) {
            return Restangular.one(`collections/${$stateParams.collection_id}`)
              .all('categories')
              .getList()
              .then(categories => {
                // We're using angular.copy here as map will strip the metadata from the restangular object

                const mappedCategories = angular.copy(categories).map(category => {
                  return {
                    id: category.id,
                    name: category.name,
                    state: 'main.shop.collection.category',
                    params: { collection_id: $stateParams.collection_id, category_id: category.id },
                  }
                })

                // Preserve metadata needed to render menu

                mappedCategories.name = categories.name
                mappedCategories.id = categories.id

                return mappedCategories
              })
          },

          tree($stateParams, $ngRedux, collectionsCategories, collectionCategories) {
            if (collectionCategories.length === 1) {
              // Don't bother showing if there's only one
              collectionCategories.length = 0 // Preserve metadata
            }

            const mapStateToService = state => ({ inCateringContext: inCateringContext(state) })
            $ngRedux.connect(mapStateToService)(this)

            // decide wether or not to show the Collections parent
            const showCollections = !configConstants.disableCollections && !this.inCateringContext

            const parents = []
            if (isWaysToShopEnabled()) {
              parents.push(shopContentParents.waysToShop)
            }
            if (showCollections) {
              parents.push(shopContentParents.collections)
            }

            return {
              current: {
                name: collectionCategories.name,
                state: 'main.shop.collection',
                params: { collection_id: $stateParams.collection_id },
              },
              parents,
              siblings: collectionsCategories,
              children: collectionCategories,
            }
          },
        },

        views: shopContentViews,
      })

      // Individual collection subcategory

      .state('main.shop.collection.category', {
        url: '/:category_id',

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams) {
            return {
              endpoint: `collections/${$stateParams.collection_id}`,
            }
          },

          tree($stateParams, collectionsCategories, collectionCategories) {
            const category = collectionCategories.find(
              category => category.id.toString() === $stateParams.category_id
            )

            return {
              current: {
                name: category.name,
                state: 'main.shop.collection.category',
                params: {
                  collection_id: $stateParams.collection_id,
                  category_id: $stateParams.category_id,
                },
              },
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                shopContentParents.collections,
                {
                  name: collectionCategories.name,
                  state: 'main.shop.collection',
                  params: { collection_id: $stateParams.collection_id },
                },
              ],
              siblings: collectionCategories,
              children: [],
            }
          },
        },

        views: shopContentViews,
      })

      .state('main.shop.offersAndCoupons', {
        url: '/offersAndCoupons',

        resolve: {
          loadCtrl: $ocLazyLoad => {
            return import(
              /* webpackChunkName: "collectionPreviewsController", webpackPrefetch: true */ './controllers/collectionPreviewsController'
            )
              .then(mod => $ocLazyLoad.load(mod.default))
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },
          collections($q, lang, Restangular) {
            function convertToCollection(name, sref) {
              return function convertToCollectionObject(items) {
                return {
                  name,
                  items,
                  sref,
                }
              }
            }

            const offers = Restangular.all('offers')
              .getList(configConstants.offersAndCoupons.queries.offers)
              .then(convertToCollection(lang.values.offers, 'main.shop.offers'))
            const coupons = Restangular.all('offers')
              .getList(configConstants.offersAndCoupons.queries.coupons)
              .then(convertToCollection(lang.values.coupons, 'main.shop.coupons'))
            return $q.all([offers, coupons]).then(([offers, coupons]) => {
              const offersAndCollections = [...offers.items, ...coupons.items]

              _offerAnalytics.viewOffers({
                offers: offersAndCollections,
                offerPageMeta: configConstants.offersAndCoupons.queries,
                offerOrigin: null,
              })

              return [offers, coupons]
            })
          },

          tree() {
            return {
              current: shopContentParents.offersAndCoupons,
              parents: !isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop],
            }
          },
        },

        views: {
          'content@main.shop': {
            template: require('./views/collectionPreviews.html'),
            controller: 'CollectionPreviewsController',
            children: [],
          },
        },
      })

      .state('main.shop.recipes', {
        url: '/recipes',
        views: shopContentViews,
        data: {
          section: 'recipes',
        },
        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($stateParams) {
            // Show subcategories unless tags or brands are applied,
            // otherwise show products

            return {
              endpoint:
                ($stateParams.tags && $stateParams.tags.length) || $stateParams.brand_name
                  ? 'recipes'
                  : null,
            }
          },

          recipeCategoriesFetched: recipeCategoriesService => recipeCategoriesService.fetch(),
          savedRecipesFetched: savedRecipesService => savedRecipesService.getSavedRecipes(),

          tree(lang, recipeCategoriesFetched, recipeCategoriesService) {
            // We must inject recipeCategoriesFetched to ensure the call to categories has been completed
            // Include recipe tags from API into tree's `current` structure
            recipesTreeStates.recipesHome.tags = recipeCategoriesService.tags

            const tree = {
              current: recipesTreeStates.recipesHome,
              children: recipeCategoriesService.rootCategories(),
              promoted: [],
              siblings: [],
            }

            if (configConstants.enableSavedRecipes && !configConstants.disableRecipes) {
              // Pass a type to determine which tile type to display
              recipesTreeStates.savedRecipes.tile = 'saved-recipes'

              // Add it to the promoted array which will display in front of the rest of the categories
              tree.promoted.push(recipesTreeStates.savedRecipes)
            }

            return tree
          },
        },
      })

      .state('main.shop.recipes.category', {
        url: '/categories/:recipe_category_id',
        views: shopContentViews,
        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($state, $stateParams, recipeCategoriesService) {
            const recipeCategory = recipeCategoriesService.getCategoryFromId(
              $stateParams.recipe_category_id
            )
            if (!recipeCategory) {
              $state.go('main.404')
            }

            // Show recipe tiles instead of recipe category tiles starting on this recipeCategory level (and deeper)
            // NOTE: The level AFTER "Recipes" is level 0
            const categoryLevelToShowRecipeTiles = configConstants.categoryLevelToShowRecipes
            return {
              // Call the 'recipe' endpoint if the following conditions are met:
              endpoint:
                recipeCategory.level >= categoryLevelToShowRecipeTiles ||
                recipeCategory.level === null ||
                recipeCategory.children.length === 0
                  ? 'recipes'
                  : null,
            }
          },
          tree(
            $q,
            lang,
            $stateParams,
            recipeCategoriesFetched,
            recipeCategoriesService,
            savedRecipesService
          ) {
            // We must inject recipeCategoriesFetched to ensure the call to categories has been completed

            savedRecipesService.getSavedRecipes()

            const recipeCategory = recipeCategoriesService.getCategoryFromId(
              $stateParams.recipe_category_id
            )
            recipeCategory.tags = recipeCategoriesService.tags

            return {
              current: recipeCategory,
              parents: [
                recipesTreeStates.recipesHome,
                ...recipeCategoriesService.getParents(recipeCategory).map(parent => ({
                  name: parent.name,
                  state: 'main.shop.recipes.category',
                  params: { recipe_category_id: parent.params.recipe_category_id },
                })),
              ],
              children: recipeCategoriesService.getChildren(recipeCategory),
              siblings: recipeCategoriesService.getSiblings(recipeCategory),
            }
          },
          onExit: () => {
            deleteJsonLd()
          },
        },
      })

      // Saved Recipes

      .state('main.shop.recipes.saved', {
        url: '/saved',
        views: shopContentViews,
        data: {
          recipesHomeState: recipesTreeStates.recipesHome,
          requireRegistered: true,
          section: 'savedRecipes',
        },
        resolve: {
          loadCtrl: loadShopContentViewsCtrl,
          src($state) {
            if (!configConstants.enableSavedRecipes) {
              $state.go('main.404')
            }

            return {
              endpoint: 'recipes/saved',
            }
          },
          tree(lang, recipeCategoriesFetched, recipeCategoriesService, savedRecipesService) {
            // We must inject recipeCategoriesFetched to ensure the call to categories has been completed

            savedRecipesService.getSavedRecipes()

            const tree = {
              current: recipesTreeStates.savedRecipes,
              siblings: recipeCategoriesService.rootCategories(),
              parents: [recipesTreeStates.recipesHome],
            }

            if (configConstants.enableSavedRecipes && !configConstants.disableRecipes) {
              tree.siblings.unshift(recipesTreeStates.savedRecipes)
            }

            return tree
          },
        },
      })

      // Featured Departments

      .state('main.shop.departments', {
        url: '/featured/:category_id?collections',
        resolve: {
          loadCtrl: $ocLazyLoad => {
            const importShopMenuController = import(
              /* webpackChunkName: "shopMenuController", webpackPrefetch: true */ './controllers/shopMenuController'
            )
            const importFeaturedDepartmentController = import(
              /* webpackChunkName: "featuredDepartmentController", webpackPrefetch: true */ './controllers/featuredDepartmentController'
            )

            return Promise.all([importShopMenuController, importFeaturedDepartmentController])
              .then(values => {
                $ocLazyLoad.load(values.map(module => module.default))
              })
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },
          featuredDepartment($stateParams, $state, configConstants, placementsService) {
            if (
              configConstants.allowPassingCollectionIDAsQueryParams?.merchandisingLandingPage &&
              placementsService.getURLPassedCollectionsList($stateParams)?.length > 0
            ) {
              return placementsService.getFeaturedDepartmentById(
                $stateParams.category_id,
                placementsService.getURLPassedCollectionsList($stateParams)
              )
            }
            return placementsService.getFeaturedDepartmentById($stateParams.category_id)
          },

          featuredDepartmentCollections(
            $state,
            $stateParams,
            configConstants,
            collectionsService,
            featuredDepartment,
            placementsService
          ) {
            const collectionList = featuredDepartment?.target?.collection_list
            if (!collectionList) {
              $state.go('main.404')
              return []
            }
            return collectionsService.getCollectionsFromSet(collectionList).then(result => {
              result.forEach(item => {
                item.featuredDepartmentID =
                  placementsService.getURLPassedCollectionsList($stateParams)?.length > 0 &&
                  configConstants.allowPassingCollectionIDAsQueryParams?.merchandisingLandingPage
                    ? $stateParams.category_id
                    : featuredDepartment.id.toString()
              })
              return result
            })
          },

          featuredDepartmentCollectionCategories(
            collectionsCategoriesService,
            featuredDepartmentCollections
          ) {
            return collectionsCategoriesService.buildFeaturedDepartmentCollectionCategories(
              featuredDepartmentCollections
            )
          },

          tree(
            $stateParams,
            catalogCategoriesService,
            collectionsCategoriesService,
            featuredDepartment,
            featuredDepartmentCollections,
            featuredDepartmentCollectionCategories
          ) {
            return {
              current: {
                name: featuredDepartment.name,
                state: 'main.shop.departments',
                placement: featuredDepartment,
              },
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                {
                  name: 'Departments',
                  state: 'main.shop.categories',
                },
              ],
              children: featuredDepartmentCollectionCategories,
              siblings: [],
            }
          },
        },

        views: shopDepartmentViews,
      })

      // 'Pseudo route' to allow retailers to load their GTM loaded personalization script which will run, send an xhr request
      // to their personalization provider (Adobe), and wait for a list of collection id's to be returned. Once this is returned
      // they can route the user to the MLP, i.e. '/shop/featured/:category_id?collections=$retailerCollectionArray'.

      // We put in a timeout in case the clients personalization script fails, in which case we route them to the default MLP
      .state('main.shop.departments.loading', {
        url: '/script-loading',
        resolve: {
          waitForRetailerScript($state, $stateParams, $timeout) {
            $timeout(
              () => {
                $state.go('main.shop.departments', {
                  category_id: $stateParams.category_id,
                })
              },
              configConstants.allowPassingCollectionIDAsQueryParams?.merchandisingLandingPage
                ? SCRIPT_TIMEOUT
                : 0
            )
            return new Promise(() => {})
          },
        },
      })

      // Individual featured department collection subcategory
      .state('main.shop.departments.featured', {
        url: '/{collection_id:int}',

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,

          categoryID($stateParams) {
            return $stateParams.category_id
          },

          src($stateParams) {
            return {
              endpoint: `collections/${$stateParams.collection_id}`,
              params: {
                // This isn't a real category, don't pass it to the API call otherwise it will
                // filter by the placement ID and return no product results
                category_id: null,
              },
            }
          },

          tree(
            $stateParams,
            categoryID,
            collectionsCategoriesService,
            featuredDepartmentCollections,
            featuredDepartmentCollectionCategories,
            featuredDepartment
          ) {
            const featuredCollection = featuredDepartmentCollectionCategories.find(
              featuredDepartmentCollection =>
                featuredDepartmentCollection.params.collection_id.toString() ===
                $stateParams.collection_id.toString()
            )

            return {
              current: {
                name: featuredCollection.name,
                state: 'main.shop.departments.featured',
                params: {
                  collection_id: $stateParams.collection_id,
                  category_id: categoryID,
                },
              },
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                {
                  name: 'Departments',
                  state: 'main.shop.categories',
                },
                {
                  name: featuredDepartment.name,
                  state: 'main.shop.departments',
                  params: {
                    category_id: categoryID,
                  },
                },
              ],
              siblings: featuredDepartmentCollectionCategories,
              children: [],
            }
          },
        },

        views: shopContentViews,
      })

      // Merchandised Landing Page

      .state('main.shop.mlp', {
        url: '/featured/:mlp_id/:mlp_slug?collections',
        resolve: {
          loadCtrl: $ocLazyLoad => {
            const importShopMenuController = import(
              /* webpackChunkName: "shopMenuController", webpackPrefetch: true */ './controllers/shopMenuController'
            )
            const importMerchandisedLandingPageController = import(
              /* webpackChunkName: "merchandisedLandingPageController", webpackPrefetch: true */ './controllers/merchandisedLandingPageController'
            )

            return Promise.all([importShopMenuController, importMerchandisedLandingPageController])
              .then(values => {
                $ocLazyLoad.load(values.map(module => module.default))
              })
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },

          async merchandisedLandingPage(
            $stateParams,
            collectionsService,
            configConstants,
            placementsService
          ) {
            const collectionIdsFromURLParams = placementsService.getURLPassedCollectionsList(
              $stateParams
            )
            const enableCollectionsURLParams =
              configConstants.allowPassingCollectionIDAsQueryParams?.merchandisingLandingPage

            const merchandisedLandingPage = await getMerchandisedLandingPage($stateParams.mlp_id)

            if (
              collectionIdsFromURLParams &&
              collectionIdsFromURLParams.length > 0 &&
              enableCollectionsURLParams
            ) {
              const collectionsFromSet = await collectionsService.getCollectionsFromSet(
                collectionIdsFromURLParams
              )

              const mlpContentFromURL = collectionsFromSet.map(collection => ({
                data: collection,
                type: 'collection',
              }))

              return {
                mlp: merchandisedLandingPage.mlp,
                mlp_content: mlpContentFromURL,
              }
            }
            return merchandisedLandingPage
          },

          merchandisedLandingPageCollectionCategories(merchandisedLandingPage) {
            return merchandisedLandingPage.mlp_content
              .filter(({ type }) => type === 'collection')
              .map(({ data: { id, display_name, name } }) => ({
                state: 'main.shop.mlp.collection',
                name: display_name || name,
                params: { collection_id: id },
              }))
          },

          tree(merchandisedLandingPage, merchandisedLandingPageCollectionCategories) {
            return {
              current: {
                name: merchandisedLandingPage.mlp.name,
                state: 'main.shop.mlp',
              },
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                {
                  name: 'Departments',
                  state: 'main.shop.categories',
                },
              ],
              children: merchandisedLandingPageCollectionCategories,
              siblings: [],
            }
          },
        },

        views: shopMerchandisedLandingPageViews,
      })

      .state('main.shop.mlp.collection', {
        url: '/{collection_id:int}',

        resolve: {
          loadCtrl: loadShopContentViewsCtrl,

          src($stateParams) {
            return {
              endpoint: `collections/${$stateParams.collection_id}`,
              params: {
                // This isn't a real category, don't pass it to the API call otherwise it will
                // filter by the placement ID and return no product results.
                category_id: null,
              },
            }
          },

          merchandisedLandingPageCollection($stateParams, merchandisedLandingPage) {
            return merchandisedLandingPage.mlp_content.find(
              ({ type, data: { id } }) => type === 'collection' && id === $stateParams.collection_id
            )?.data
          },

          tree(
            merchandisedLandingPage,
            merchandisedLandingPageCollection,
            merchandisedLandingPageCollectionCategories
          ) {
            const { display_name: displayName, name } = merchandisedLandingPageCollection

            return {
              current: {
                name: displayName || name,
                state: 'main.shop.mlp.collection',
              },
              parents: [
                ...(!isWaysToShopEnabled() ? [] : [shopContentParents.waysToShop]),
                {
                  name: 'Departments',
                  state: 'main.shop.categories',
                },
                {
                  name: merchandisedLandingPage.mlp.name,
                  state: 'main.shop.mlp',
                },
              ],
              siblings: merchandisedLandingPageCollectionCategories,
              children: [],
            }
          },
        },

        views: shopContentViews,
      })

      // Flyer - Flipp

      .state('main.shop.flyerFlipp', {
        cache: false,
        url: '/flyers',
        resolve: {
          loadCtrl: $ocLazyLoad => {
            const importFlyerFlippController = import(
              /* webpackChunkName: "flyerFlippController", webpackPrefetch: true */ './controllers/flyerFlippController'
            )

            return Promise.all([importFlyerFlippController])
              .then(values => {
                $ocLazyLoad.load(values.map(module => module.default))
              })
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },
        },
        views: shopFlyerFlippViews,
      })
  })
  .run(($templateCache, offerAnalytics) => {
    _offerAnalytics = offerAnalytics

    $templateCache.put('shopMenu.desktop.html', require('./views/shopMenu.desktop.html'))
    $templateCache.put('shopMenu.mobile.html', require('./views/shopMenu.mobile.html'))
    $templateCache.put('shopPagination.html', require('./views/shopPagination.html'))
    $templateCache.put('shopCells.html', require('./views/shopCells.html'))
    $templateCache.put('shopSubcategories.html', require('./views/shopSubcategories.html'))
    $templateCache.put('recipeCategories.html', require('../recipes/recipeCategories.html'))
    $templateCache.put('cellContainer.html', require('../shared/cellContainer/cellContainer.html'))
    $templateCache.put('categoryCard.html', require('../shared/categoryCard/categoryCard.html'))
    $templateCache.put(
      'pagination-modified-uibootstrap-desktop.html',
      require('../shared/pagination/pagination-modified-uibootstrap-desktop.html')
    )
    $templateCache.put(
      'pagination-modified-uibootstrap-mobile.html',
      require('../shared/pagination/pagination-modified-uibootstrap-mobile.html')
    )
  })
