/* eslint-disable max-lines */

import _ from 'lodash'
import debounce from 'lodash/debounce'
import angular from 'angular'
import { CART_TYPES } from '@app/angular/constants/cart'
import {
  CHECKOUT_STEP_IDS,
  FULFILLMENT_TYPES,
  RESERVATION_BREAKDOWN,
  RESET_CHECKOUT_STEPS,
  EDIT_CHECKOUT_STEP,
  CLOSE_CHECKOUT_STEPS_SETTLED,
} from '@app/angular/constants/checkout'
import { MEMBERSHIP_STATUSES } from '@app/angular/constants/express'
import { SHOPPING_MODES } from '@instacart/enterprise-services/shopping-list/cart/constants'
import template from './checkoutSteps.html'

class CheckoutSteps {
  constructor(
    $filter,
    $q,
    $rootScope,
    $scope,
    $timeout,
    addressList,
    addressValidationService,
    api,
    alcoholValidation,
    auth,
    cartService,
    checkoutAnalytics,
    checkoutService,
    compareCartService,
    config,
    express,
    instacartCustomerLinkModalService,
    lang,
    mobileService,
    paymentGateway,
    reservationService,
    storesService
  ) {
    Object.assign(this, {
      $filter,
      $q,
      $rootScope,
      $scope,
      $timeout,
      addressList,
      addressValidationService,
      alcoholValidation,
      api,
      auth,
      cartService,
      checkoutAnalytics,
      checkoutService,
      compareCartService,
      config,
      express,
      hasOpenSection: false,
      lang,
      mobileService,
      paymentGateway,
      paymentService: null,
      reservationService,
      storesService,
      stores: [],
      instacartCustomerLinkModalService,
      hasSelectedCateringStore: false,
    })
    this.brokenDownStepsOrder = this._breakDownReservationStep()
    this.steps = this.initializeSteps()
    this.stepNames = Object.keys(this.steps)
    this.paymentIsInitialStep = this.initialStep === CHECKOUT_STEP_IDS.PAYMENT
  }

  $onInit() {
    const { $q, $scope, api, initialStep } = this

    const initializePayment = payment => {
      this.payment = payment

      // In this case of modify order, set the previous payment instrument into payment
      const existingPaymentInstrument = this.cart.order?.payment_instruments?.[0]
      const existingPaymentType = this.cart.order?.payment_type
      const existingGiftCards = this.cart.order?.gift_cards

      if (existingPaymentType && this.payment) {
        this.payment.payment_type = existingPaymentType
        this.payment[existingPaymentType] = existingPaymentInstrument
      }

      if (existingGiftCards && this.payment) {
        this.payment.gift_cards = existingGiftCards
      }

      $scope.$watch(
        () => this.payment.payment_type,
        () => {
          const { payment_type: paymentType } = this.payment
          if (paymentType) {
            this.paymentService = this.paymentGateway.getService(paymentType)
          }
          this.onPaymentUpdate({ payment })
        }
      )

      $scope.$watch(
        () => this.paymentService?.status?.ready,
        () => this.validate()
      )

      $scope.$watch(
        () => this.reservationService.reservation,
        () => {
          this.validate()

          const scheduleStep = this.steps[CHECKOUT_STEP_IDS.SCHEDULE]
          const isCurrentStepBeforeScheduleStep =
            this.currentStep && this.currentStep.order < scheduleStep.order
          if (!this._isScheduleValid() && !isCurrentStepBeforeScheduleStep) {
            this.editStep(CHECKOUT_STEP_IDS.SCHEDULE)
          }
        }
      )
    }

    const initializeStores = stores => {
      this.stores = stores
    }

    const initializeOrderDeliveryInstructions = () => {
      this.checkoutService.setOrderDeliveryInstructions(
        this.$rootScope.modifyingOrder
          ? this.cartService.getModifyOrderDeliveryInstruction().leaveUnattended
          : this.cartService.getOrderDeliveryInstruction().leaveUnattended
      )
    }

    this.$rootScope.isAppLoading = true

    const linkAccount = () => {
      const shouldLinkAccount =
        this.config.instacart.enableAccountLinking && !this.auth.user.ic_linked

      if (shouldLinkAccount) {
        return this.instacartCustomerLinkModalService
          .hasAccountBeenCreated()
          .then(() => Promise.resolve(true))
          .catch(() => Promise.resolve(false))
      }

      return Promise.resolve(shouldLinkAccount)
    }

    // Fetching the initial data the new checkout page needs
    const fetchInitialInformation = [
      this.getProfile(),
      this.fetchStores(),
      api.getCartPayment({ cart_id: this.getCartId() }),
      this.checkoutService.getSelectedDeliveryAddress(!this.isModifyingOrder()),
      linkAccount(),
    ]

    const setInitialAddress = address => {
      if (this.fulfillmentType === FULFILLMENT_TYPES.DELIVERY) {
        if (!this.isModifyingOrder()) {
          return this.handleInitialDeliveryAddress(address || this.auth.getAddressFromContext())
        }

        const deliveryAddress = {
          ...this.cartService.cart.order?.delivery_address,
          phone_number: this.cartService.cart.order?.ext_data?.phone_number || address.phone_number,
        }

        return this.handleInitialDeliveryAddress(deliveryAddress)
      }
    }

    const initializeCheckout = () => {
      return $q
        .all(fetchInitialInformation)
        .then(([, , payment, address, isAccountLinked]) => {
          initializeOrderDeliveryInstructions()
          initializePayment(payment)
          initializeStores(this.storesService.stores)

          return { address, isAccountLinked }
        })
        .then(({ address, isAccountLinked }) => {
          setInitialAddress(address)
          return isAccountLinked
        })
        .then(isAccountLinked => this._showModals(isAccountLinked))
        .then(this.reservationService.fetchWithCart(this.getCartId()))
        .then(() => this._validateCartAndOpenNextSection())
        .catch(err => this.handleError(err))
        .finally(() => {
          if (!initialStep) {
            this.$rootScope.isAppLoading = false
          } else {
            // Sometimes the delivery section will force itself open and then timeslots is disabled
            // This ensures a loading mask doesn't get stuck forever over the checkout
            this.$timeout(() => {
              if (this.$rootScope.isAppLoading) {
                this.$rootScope.isAppLoading = false
              }
            }, 5000)
          }

          this.isReady = true
        })
    }

    initializeCheckout()

    /**
     * Whenever the *_isValid* getter value changes calls the
     * *onValidateChange* prop function with the new validation
     * status so the parent can decide what to do
     */
    $scope.$watch(
      () => this._isValid,
      () => this.onValidateChange({ isValid: this._isValid })
    )

    /**
     * Once all checkout sections have closed and none are forced open, broadcast an event
     * This can be used to perform actions once the checkout is in a stable state
     */
    const closeStateSettled = debounce(isOpen => {
      if (!isOpen && this.isReady) {
        this.$scope.$broadcast(CLOSE_CHECKOUT_STEPS_SETTLED)
      }
    }, 1000)

    this.$scope.$on(RESET_CHECKOUT_STEPS, () => closeStateSettled(false))
    this.$scope.$on(EDIT_CHECKOUT_STEP, () => closeStateSettled(true))

    /*  Open initial section once checkout has settled */
    this.$scope.$on(CLOSE_CHECKOUT_STEPS_SETTLED, this.openInitialStep)
  }

  isModifyingOrder() {
    return this.$rootScope.modifyingOrder || this.$rootScope.modifyingInstacartOrder
  }

  openInitialStep = () => {
    const { initialStep } = this

    // If having an initial step, open the section after a delay (ensure all other sections close)
    if (initialStep) {
      this.editStep(initialStep)
      this.$rootScope.isAppLoading = false
      this.initialStep = null
    }
  }

  _showModals = isAccountLinked => {
    const showLinkingAccountModal =
      !isAccountLinked && this.config.instacart.enableAccountLinking && !this.auth.user.ic_linked

    const modals = [
      this.alcoholValidation.shouldShowModal() && this.alcoholValidation.open(),
      showLinkingAccountModal &&
        this.instacartCustomerLinkModalService.openModal({
          linkLater: false,
          forceSkippableState: true,
        }),
    ]

    return this.$q.all(modals).catch(() => this.onCheckoutExit())
  }

  _validateCartAndOpenNextSection = () => {
    this.validateCart(true)
    this.validate(false)
    this.openNextSection()
  }

  /**
   * Handles the initial delivery address fetching by
   * setting the delivery address object and validating
   * its postal code
   *
   * @param {object} address
   * @returns {undefined}
   */
  handleInitialDeliveryAddress(address) {
    this.checkoutService.setDeliveryAddress(address)

    if (!this._isDeliveryAddressValid(address)) {
      this.checkoutService.stepsStatuses[CHECKOUT_STEP_IDS.DELIVERY].valid = false
      this.validateCart(true)
      this.validate()
      this.openNextSection()
      return
    }

    this.validateDeliveryPostalCode(address.postal_code)
      .then(isValid => {
        const deliveryStatus = this.checkoutService.stepsStatuses[CHECKOUT_STEP_IDS.DELIVERY]

        this.validate()
        if (!isValid) {
          deliveryStatus.valid = isValid
          return
        }

        deliveryStatus.valid = true
        this.validateCart(true)
      })
      .catch(() => (this.checkoutService.stepsStatuses[CHECKOUT_STEP_IDS.DELIVERY].valid = false))
      .finally(() => this.openNextSection())
  }

  /**
   * Handles errors from the delivery-form component by
   * setting the *valid* status of the delivery step to false
   */
  handleDeliveryAddressError() {
    this.checkoutService.stepsStatuses[CHECKOUT_STEP_IDS.DELIVERY].valid = false
    this.openNextSection()
  }

  _handleAddressAppliedSingleBanner() {
    const validateAndOpenNext = () => {
      this.checkoutService.stepsStatuses[CHECKOUT_STEP_IDS.DELIVERY].valid = true
      this.validate()
      this.openNextSection()
      this.validateCart()
    }

    if (!this.reservationService.reservation) {
      validateAndOpenNext()
      return
    }

    this.reservationService
      .deleteReservation({ cart_id: this.getCartId() })
      .finally(() => validateAndOpenNext())
  }

  _handleAddressAppliedMultiBanner() {
    this.checkoutService.stepsStatuses[CHECKOUT_STEP_IDS.DELIVERY].valid = true
    this.checkoutService.stepsStatuses[CHECKOUT_STEP_IDS.BANNER].valid = false
    this.validate()
    this.openNextSection()
    this.validateCart()
  }

  /**
   * Executes when a valid address is selected in delivery-form
   * component. It sets the delivery step status to true, validates
   * the checkout form as a whole and open the next invalid/empty step
   */
  handleAddressApplied(address) {
    this.checkoutService.setDeliveryAddress(address)

    if (this.config.multipleBanners.enabled) {
      // Need to check if multiple banners deliver to this address
      this.storesService
        .fetchBanners({ delivery_postal_code: address.postal_code })
        .then(banners => {
          if (banners.items.length === 1) {
            this._handleAddressAppliedSingleBanner()
          } else if (banners.items.length > 1) {
            this._handleAddressAppliedMultiBanner()
          }
        })
    } else {
      this._handleAddressAppliedSingleBanner()
    }
  }

  /**
   * Validates a postal code for delivery
   *
   * @param {object} postalCode
   * @returns {Promise}
   */
  validateDeliveryPostalCode(postalCode) {
    return this.storesService
      .fetch({
        delivery_postal_code: postalCode,
        show_delivery: true,
        retailer_slug: this.auth.user.context.retailer_slug,
      })
      .then(stores => {
        return stores !== undefined && stores.length > 0
      })
      .catch(() => this.handleInvalidPostalCodeError())
  }

  /**
   * Fetches the user profile
   *
   * @returns { Promise }
   */
  getProfile() {
    const initializeContact = () => {
      const {
        primary_number_key,
        delivery_address: { first_name, last_name, address1, postal_code },
      } = this.auth.user
      const phone_number = this.auth.user[primary_number_key]

      this.checkoutService.setContact({
        primary_number_key,
        phone_number,
        first_name,
        last_name,
        address1,
        postal_code,
      })

      this.onContactUpdate(this.checkoutService.contact)
    }

    return this.auth.getProfile().then(() => initializeContact())
  }

  /**
   * Excecutes after the contact information
   * is successfully saved.
   *
   * It updates the auth object, validates the checkout form and
   * open the next invalid/empty section
   *
   * @param { boolean } fetch - Defines if fetching the profile is necessary
   *
   * @returns { Promise }
   */
  handleContactInfoSave(fetch, contact) {
    // Clear out any contact save errors
    this.checkoutService.setError(null)

    const sync = fetch ? this.getProfile() : this.$q.resolve()

    return sync.then(() => {
      this.checkoutService.setContact(contact)
      this.validate()
      this.validateCart()
      this.openNextSection()
    })
  }

  /**
   * Passes on the order delivery information
   * @param { boolean | undefined } leaveUnattended - Whether product should be left at door or not
   *        (If not provided, it will clear instructions, to force re-open the instructions section)
   *
   * @returns { Promise }
   */
  handleOrderDeliveryInstructionsSave(leaveUnattended) {
    const hasAlcohol = this.cartService.getHasAlcohol()
    this.checkoutService.setError(null)

    if (typeof leaveUnattended === 'boolean') {
      this.checkoutService.setOrderDeliveryInstructions(leaveUnattended && !hasAlcohol)
    } else {
      this.checkoutService.clearOrderDeliveryInstructions()
    }

    this.validate()
    this.validateCart()
    this.openNextSection()
  }

  /**
   * Fetches the stores based fulfillment type
   *
   * @returns { Promise }
   */
  fetchStores() {
    const params = {}
    if (this.fulfillmentType !== FULFILLMENT_TYPES.INSTORE) {
      params.show_ecommerce = true
    }

    if (this.cartService.getCurrentCartType() === CART_TYPES.CATERING) {
      params.show_catering = true
    } else if (this.fulfillmentType === FULFILLMENT_TYPES.PICKUP) {
      params.show_pickup = true
    }

    return this.storesService.fetch(params)
  }

  /**
   * Getter for the validation status of the checkoutSteps
   * based on the current user context
   *
   * @returns { Boolean }
   */
  get _isValid() {
    const { isCartValidated, stepsStatuses } = this.checkoutService
    const isEditing = !!Object.values(this.steps).find(step => step.editMode)
    if (isEditing) return false

    if (this.fulfillmentType === FULFILLMENT_TYPES.PICKUP) {
      return (
        isCartValidated &&
        stepsStatuses[CHECKOUT_STEP_IDS.CONTACT].valid &&
        stepsStatuses[CHECKOUT_STEP_IDS.PICKUP].valid &&
        stepsStatuses[CHECKOUT_STEP_IDS.SCHEDULE].valid &&
        stepsStatuses[CHECKOUT_STEP_IDS.PAYMENT].valid
      )
    }

    return (
      isCartValidated &&
      stepsStatuses[CHECKOUT_STEP_IDS.DELIVERY].valid &&
      stepsStatuses[CHECKOUT_STEP_IDS.SCHEDULE].valid &&
      stepsStatuses[CHECKOUT_STEP_IDS.PAYMENT].valid &&
      (!this.orderDeliveryInstructionsStep ||
        stepsStatuses[CHECKOUT_STEP_IDS.ORDER_DELIVERY_INSTRUCTIONS].valid)
    )
  }

  /**
   * Returns an array of steps with the correct editMode state
   *
   * @returns { object[] }
   */
  initializeSteps() {
    const steps = {}

    this.filteredSteps.forEach(step => {
      steps[step.name] = step
      steps[step.name].editMode = false
      steps[step.name].disabled = false
    })

    this.orderDeliveryInstructionsStep = steps[CHECKOUT_STEP_IDS.ORDER_DELIVERY_INSTRUCTIONS]

    return steps
  }

  /**
   * Resets all steps *editMode* to false and
   * Sets the given step to edit mode
   *
   * @param { string } stepID
   *
   * @returns { undefined }
   */
  editStep(stepID) {
    this.resetSteps()
    this.steps[stepID].editMode = true
    this.onStepEditMode({ stepID })
    this.$scope.$broadcast(EDIT_CHECKOUT_STEP)
  }

  onPaymentSave(isValid, hasBeenEdited) {
    this.validate(hasBeenEdited)
    this.openNextSection()
  }

  /**
   * Allows a step to abort its edit
   *
   * @returns { undefined }
   */
  abortEdit() {
    this.resetSteps()
  }

  /**
   * Returns an array of checkout steps filtered by the current
   * fulfillment type and in the same order as defined in the config file
   *
   * @returns { object[] }
   */
  get filteredSteps() {
    const fulfillmentType = this.auth.getContext().intent

    return this.orderedSteps.filter(step => {
      switch (step.name) {
        case CHECKOUT_STEP_IDS.PICKUP:
        case CHECKOUT_STEP_IDS.CONTACT:
          return fulfillmentType === FULFILLMENT_TYPES.PICKUP
        case CHECKOUT_STEP_IDS.BANNER:
          return (
            fulfillmentType === FULFILLMENT_TYPES.DELIVERY && this.config.multipleBanners.enabled
          )
        case CHECKOUT_STEP_IDS.DELIVERY:
        case CHECKOUT_STEP_IDS.ORDER_DELIVERY_INSTRUCTIONS:
          return fulfillmentType === FULFILLMENT_TYPES.DELIVERY
        default:
          return step
      }
    })
  }

  /**
   * Takes an array of steps with the reservation config object and converts
   * it into the steps defined in 'RESERVATION_BREAKDOWN'
   *
   * @param { array } steps
   *
   * @returns { array } steps
   */
  _breakDownReservationStep() {
    const steps = this.config.newCheckoutFlow.checkoutPage.stepsOrder
    const validStepNames = Object.values(CHECKOUT_STEP_IDS)
    const deepCopySteps = _.cloneDeep(
      steps.filter(step => {
        const hasStep = validStepNames.includes(step.name)
        if (!hasStep) console.warn(`Unknown checkout step from config: ${step.name}`)
        return hasStep
      })
    )
    const reservation = deepCopySteps.find(step => step.name === CHECKOUT_STEP_IDS.RESERVATION)
    // If there is no config object for reservation just return the steps
    if (!reservation) return deepCopySteps
    // Go through the steps and increment the order of all the steps
    // that come after the reservation
    deepCopySteps.forEach(
      step => (step.order += step.order > reservation.order ? RESERVATION_BREAKDOWN.length - 1 : 0)
    )
    // Add the steps that a reservation consists of
    RESERVATION_BREAKDOWN.forEach((item, i) =>
      deepCopySteps.push({ ...item, order: reservation.order + i })
    )
    // Remove the reservation object from the steps and return it
    return deepCopySteps.filter(item => item !== reservation)
  }

  /**
   * Returns an array of ordered steps as defined in the client config
   *
   * @returns { object[] }
   */
  get orderedSteps() {
    return this.brokenDownStepsOrder.sort(
      (currentStep, nextStep) => currentStep.order - nextStep.order
    )
  }

  /**
   * Returns the title for the cart section component
   *
   * @returns { string }
   */
  get cartTitle() {
    const cartSize = this.cartService.countItems()
    const { item } = this.lang.values

    return `${cartSize} ${this.$filter('pluralize')(item, cartSize)}`
  }

  /**
   * Returns an array of ordered steps as defined in the client config
   *
   * @returns { object }
   */
  get currentStep() {
    return Object.values(this.steps).find(step => step.editMode)
  }

  /**
   *  Sets a cart update message object in the cartService
   *
   * @param {number} cartChangeSize
   */
  showCartUpdatedMessage(cartChangeSize) {
    const { cartUpdated, cartUpdateMessage, item, addedTo, removedFrom } = this.lang.values
    const changeSize = Math.abs(cartChangeSize)
    const changeType = cartChangeSize > 0 ? addedTo : removedFrom
    const itemText = this.$filter('pluralize')(item, changeSize)
    const body = this.lang.template(cartUpdateMessage, {
      changeSize,
      changeType,
      item: itemText,
    })

    this.cartService.updateMessage = {
      title: cartUpdated,
      body,
    }
  }

  /**
   * Handles errors by displaying the error message at the top of the page
   *
   * @param { object } error
   */
  handleError(error) {
    this.checkoutService.setError(error)
    this.checkoutService.logCheckoutErrorAnalytics(error)

    document.querySelector('.checkout-page').scrollTop = 0
  }

  /**
   * Resets all the steps editMode to false
   */
  resetSteps() {
    this.filteredSteps.forEach(step => (step.editMode = false))
    this.$scope.$broadcast(RESET_CHECKOUT_STEPS)
  }

  /**
   * Validates the data of each checkout step depending on the user current fulfillment type sections
   *
   * @param { Boolean } isUserInitiated:      Optional: True if user initiated the validate action
   * @returns { Boolean }
   */
  validate(isUserInitiated = true) {
    const {
      SCHEDULE,
      PAYMENT,
      PICKUP,
      CONTACT,
      DELIVERY,
      ORDER_DELIVERY_INSTRUCTIONS,
    } = CHECKOUT_STEP_IDS

    this.checkoutService.stepsStatuses[SCHEDULE].valid = this._isScheduleValid()
    this.checkoutService.stepsStatuses[PAYMENT].valid = this._isPaymentValid()

    if (this.fulfillmentType === FULFILLMENT_TYPES.PICKUP) {
      this.checkoutService.stepsStatuses[PICKUP].valid = this._isPickupStoreValid(isUserInitiated)
      this.checkoutService.stepsStatuses[CONTACT].valid = this._isContactInformationValid()
    }

    if (this.fulfillmentType === FULFILLMENT_TYPES.DELIVERY) {
      this.checkoutService.stepsStatuses[DELIVERY].valid = this._isDeliveryAddressValid(
        this.checkoutService.deliveryAddress
      )

      this.checkoutService.stepsStatuses[
        ORDER_DELIVERY_INSTRUCTIONS
      ].valid = this._isOrderDeliveryInstructionsValid()
    }

    this._updateDisabledStatus()

    return this._isValid
  }

  /**
   * Validates a given *address* object
   *
   * @param {object} address
   * @returns {boolean}
   */
  _isDeliveryAddressValid(address) {
    return this.addressValidationService.isAddressComplete(address)
  }

  /**
   * Updates the disabled status of each checkout step
   * If a step is invalid/incomplete all the following steps will be
   * disabled
   *
   * @returns {undefined}
   */
  _updateDisabledStatus() {
    let disableNextSteps = false
    const { stepsStatuses } = this.checkoutService

    Object.keys(this.steps).forEach(step => {
      this.steps[step].disabled = disableNextSteps

      if (!stepsStatuses[step].valid) {
        disableNextSteps = true
      }
    })
  }

  /**
   * Set the next invalid or empty section to edit mode
   *
   * @returns { undefined }
   */
  openNextSection() {
    const nextStep = this.getNextIncompleteStep()
    this.resetSteps()
    this._updateDisabledStatus()

    if (
      !nextStep ||
      (nextStep.name === this.currentStep?.name && nextStep?.name === CHECKOUT_STEP_IDS.PAYMENT)
    ) {
      return
    }

    this.editStep(nextStep.name)
  }

  /**
   * Returns the next incomplete step
   *
   * @returns { object }
   */
  getNextIncompleteStep() {
    const { stepsStatuses } = this.checkoutService

    return this.filteredSteps.find(({ name }) => stepsStatuses[name].valid === false)
  }

  /**
   * Executes after the user selects a store a
   * *hasChanged* and *store* params are passed from the pickup-form child component
   * so this method can decide when to do a cart compare, cart validate etc
   *
   * @param { boolean } hasChanged
   * @param { object } store - selected store
   *
   * @returns { undefined }
   */
  handlePickupApply(hasChanged, store) {
    const validateAndOpenNextSection = () => {
      this.validate()
      this.openNextSection()
    }
    this.hasSelectedCateringStore = true

    if (hasChanged) {
      return this.compareCartService
        .compareCart(true, 'store', { store_id: store.id })
        .then(({ shouldRecalculateFees }) => {
          this.checkoutService.isCartValidated = false
          this.checkoutService.handleCartCompareChange(shouldRecalculateFees)

          this.auth
            .update({ store_id: store.id, has_changed_store: true })
            .then(this.cartService.fetch)
            .then(() => this.auth.sendUserUpdatedStore(this.auth.user))
            .then(() => this.auth.sendAvailableStoreContexts(this.auth.user))
            .then(() => this.checkoutAnalytics.editFulfillmentOptions({ success: true }))
            .then(() => this.reservationService.deleteReservation({ cart_id: this.getCartId() }))
            .then(validateAndOpenNextSection)
            .catch(error => {
              this.checkoutAnalytics.editFulfillmentOptions({ success: false })
              this.checkoutService.logCheckoutErrorAnalytics(error)
            })
        })
        .catch(validateAndOpenNextSection)
    }
    validateAndOpenNextSection()
  }

  // Step validators

  /**
   * Validates the pickup store section data
   * @param { Boolean } isUserInitiated:      Optional: True if user initiated the validate action
   *
   * @returns { Boolean }
   */
  _isPickupStoreValid(isUserInitiated = true) {
    const {
      user: { store },
    } = this.auth
    if (
      (!this.auth.user.has_changed_store &&
        parseInt(store?.id, 10) === parseInt(this.config.defaultStoreID, 10)) ||
      (this.cartService.getCurrentCartType() === CART_TYPES.CATERING &&
        !this.hasSelectedCateringStore &&
        this.config.alwaysOpenStoreSelectorCatering)
    ) {
      return false
    }
    if (isUserInitiated) {
      return !!store?.id
    }
    return !!store?.id && parseInt(store?.id, 10) !== parseInt(this.config.defaultStoreID, 10)
  }

  /**
   * Validates the contact information data
   *
   * @returns { Boolean }
   */
  _isContactInformationValid() {
    const { first_name, last_name, phone_number } = this.checkoutService.contact

    if (!first_name || !last_name || !phone_number) return false

    return (
      first_name.length > 0 &&
      first_name.length <= this.config.maxNameLength &&
      last_name.length > 0 &&
      last_name.length <= this.config.maxNameLength &&
      phone_number.length > 0
    )
  }

  /**
   * Validates the delivery instruction data
   *
   * @returns { Boolean }
   */
  _isOrderDeliveryInstructionsValid() {
    const { orderDeliveryInstructions } = this.checkoutService
    return (
      !this.orderDeliveryInstructionsStep ||
      typeof orderDeliveryInstructions?.leaveUnattended === 'boolean'
    )
  }

  /**
   * Validates the timeslot data
   *
   * @returns { Boolean }
   */
  _isScheduleValid() {
    return (
      this.reservationService?.reservation?.id !== undefined &&
      !this.reservationService?.reservation?.timeslot?.during_blackout &&
      !this.reservationService?.reservation?.timeslot?.unavailable
    )
  }

  /**
   * Validates the payment data
   *
   * @returns { Boolean }
   */
  _isPaymentValid() {
    const { payment_type: paymentType } = this.payment || {}

    if (!paymentType) return false

    const gatewayService = this.paymentGateway.getService(paymentType)

    return gatewayService.status?.ready || false
  }

  /**
   * Returns if auth is deferred (eg. for >7 day payments)
   *
   * @returns { Boolean }
   */
  isDeferredAuth() {
    return this.reservationService?.reservation?.full_auth_will_defer
  }

  /**
   * Checks the cart validate payload for values that are missing before the
   * cart can be validated though the cart service.
   *
   * @returns { Boolean }
   */
  checkValidationPayloadForEmptyValues(cartValidationPayload) {
    const hasEmptyValue = Object.values(cartValidationPayload).some(
      value => typeof value === 'undefined' || value === null
    )

    const { delivery_address } = cartValidationPayload
    // If the delivery address exists, check if the required fields are set.
    const hasEmptyDeliveryAddress =
      delivery_address && !(delivery_address.address1 && delivery_address.postal_code)

    return hasEmptyValue || hasEmptyDeliveryAddress
  }

  /**
   * Calls the cart validate endpoint whenever the following information is updated
   *   *delivery*: delivery address or timeslot
   *   *pickup*: store or pickup
   *
   * The endpoint should only be called when all the information is filled
   *
   * @returns {undefined}
   */
  validateCart(withFees = false) {
    const cartValidateInfo = this.checkoutService.getValidationPayload(this.fulfillmentType)
    const hasEmptyValue = this.checkValidationPayloadForEmptyValues(cartValidateInfo)
    const { ic_linked } = this.auth.user
    const { enableAccountLinking } = this.config.instacart

    if (!hasEmptyValue && (!enableAccountLinking || ic_linked)) {
      const payload = { with_fees: withFees, ...cartValidateInfo }

      if (this.orderDeliveryInstructionsStep) {
        const { orderDeliveryInstructions } = this.checkoutService
        payload.leave_unattended = Boolean(
          // this.cart.order?.leave_unattended is defined only for modify case, otherwise...
          this.cartService.cart.order?.leave_unattended ??
            (orderDeliveryInstructions?.leaveUnattended && !this.cartService.getHasAlcohol())
        )
      }

      this.cartService
        .validateCart(payload)
        .then(() => this._handleValidateCartSuccess())
        .catch(err => this._handleValidateCartError(err))
    }
  }

  /**
   * Handles timeslot selection
   */
  handleScheduleFormSave() {
    this.validate()
    this.openNextSection()
    const { first_name, last_name, phone_number } = this.checkoutService.contact

    if (
      this.fulfillmentType === SHOPPING_MODES.PICKUP &&
      (!first_name || !last_name || !phone_number)
    ) {
      return
    }

    this.checkoutService.isCartValidated = false
    this.validateCart(this.checkoutService.validateWithFees)
  }

  /**
   * Handles successful validate cart calls
   */
  _handleValidateCartSuccess() {
    this.checkoutService.validateWithFees = false
    this.checkoutService.isCartValidated = true
  }

  /**
   * Handles error validate cart calls
   */
  _handleValidateCartError(error) {
    this.checkoutService.logCheckoutErrorAnalytics(error)
    this.checkoutService.isCartValidated = false

    /**
     * If the express feature is enabled and the user is currently on Express Trial, on cart validation errors,
     * we fetch the user current subscription status because their trial might have been canceled.
     * In the future, we need to discuss with Connect the possibility of using a specific error code for this scenario
     */
    if (
      this.config.icExpress.enabled &&
      this.express.getMembershipStatus() === MEMBERSHIP_STATUSES.TRIAL
    ) {
      this.express.getCurrentSubscription(true)
      this.editStep(CHECKOUT_STEP_IDS.SCHEDULE)
    }

    if (error === this.cartService.TIMESLOT_UNAVAILABLE_ERROR) {
      this.editStep(CHECKOUT_STEP_IDS.SCHEDULE)
    }
  }

  getCartId() {
    return this.cartService.getCartId(this.cartService.getCurrentCartType())
  }

  getCartItemsByType = type => this.cartService.getCartTypeToItemsMap()[type]

  /**
   * Callback method that is invoked when the user cancel the address form
   * edit. It validates the checkout steps and opens the next invalid/empty
   * section
   *
   * @returns { undefined }
   */
  onCancelAddressEdit() {
    this.validate()
    this.openNextSection()
  }

  onChangeBanner() {
    this.checkoutService.stepsStatuses[CHECKOUT_STEP_IDS.BANNER].valid = true
    this.openNextSection()
  }
}

export default angular.module('CheckoutStepsModule', []).component('checkoutSteps', {
  bindings: {
    cart: '<',
    fulfillmentType: '<',
    initialStep: '<',
    onValidateChange: '&', // prop method that gets called whenever the *isValid* attribute changes
    onStepEditMode: '&',
    onPaymentUpdate: '&',
    onContactUpdate: '&',
    onCheckoutExit: '&',
    cartType: '<',
  },
  template,
  controller: CheckoutSteps,
})
