import angular from 'angular'
import { ACTIONS } from '@app/angular/constants/recaptcha'

export default angular
  .module('DefaultPaymentServiceModule', [])
  .config(paymentGatewayProvider => {
    paymentGatewayProvider.registerGateway({
      name: 'Cash',
      key: 'cash',
      serviceName: 'defaultPayments',
      noSettingsRequired: true,
    })

    paymentGatewayProvider.registerGateway({
      name: 'Credit Card',
      key: 'credit',
      serviceName: 'defaultPayments',
      noSettingsRequired: true,
    })

    paymentGatewayProvider.registerGateway({
      name: 'Debit',
      key: 'debit',
      serviceName: 'defaultPayments',
      noSettingsRequired: true,
    })
  })

  .service(
    'defaultPayments',
    function defaultPayments(
      $q,
      $rootScope,
      $state,
      $timeout,
      Restangular,
      analytics,
      checkoutAnalytics,
      config
    ) {
      const service = this

      service.status = { loaded: false, ready: true }

      service.checkout = function (payment, optionalData = {}) {
        return Restangular.one('orders')
          .post(null, payment)
          .then(order => {
            const { postCheckoutRoute } = optionalData
            // don't call analytics on a modified order
            if (!$rootScope.modifyingOrder) {
              analytics.logCheckoutStep(5, payment.payment_type, order.order_items)
              analytics.logCheckoutCompletion(order)
              analytics.logCriteoOrderConfirmation({ id: order.id, orderItems: order.order_items })
              checkoutAnalytics.placeOrderClick({ order, success: true })
              analytics.logFBPurchase(order)
            }

            $state.go(postCheckoutRoute || 'main.checkout.confirmation', { orderId: order.id })

            return order
          })
          .catch(err => {
            checkoutAnalytics.placeOrderClick({ order: null, success: false })
            throw err
          })
      }

      /**
       * Shared logic for initializing recaptcha
       *
       * @param {function | object} recaptchaSetupCallbackOrObject
       *        - Function that accepts a setup object with attribs values for <recaptcha-checkbox>.
       *        - Or an object that will have that setup object merged into it.
       *        - It will not be called or updated if recaptcha is off for payments.
       *
       * @returns {Promise<null | object>}
       *          - Returns a promise that resolves with the recaptcha info, or null if not enabled
       */
      service.initializeRecaptcha = function (recaptchaSetupCallbackOrObject) {
        return $q((resolve, reject) => {
          const { paymentsEnabled, siteKey } = config.googleRecaptcha || {}
          if (paymentsEnabled) {
            const recaptchaException = new Error('Recaptcha error - payments') // For potential errors

            // Setup object with angular attribs for the recaptcha component
            const setupObj = {
              show: true,
              action: ACTIONS.CHECKOUT,
              onRecaptchaSuccess: token => resolve({ token, siteKey }),
              onRecaptchaExpired: () => {
                recaptchaException.isRecaptchaError = true
                reject(recaptchaException)
              },
            }

            // If recaptchaSetupCallbackOrObject is a function, call it, and IF it returns a promise
            // listen for reject, pass on that reject. This is useful for modal dismissal.
            if (typeof recaptchaSetupCallbackOrObject === 'function') {
              const optionalPromise = recaptchaSetupCallbackOrObject(setupObj)
              if (typeof optionalPromise?.catch === 'function') {
                optionalPromise.catch(() => {
                  recaptchaException.isRecaptchaAbort = true
                  reject(recaptchaException)
                })
              }
            } else {
              Object.assign(recaptchaSetupCallbackOrObject, setupObj)
            }
          } else {
            resolve(null)
          }
        })
      }
    }
  )
