import _ from 'lodash'
import angular from 'angular'

import {
  CONDITION_TYPES,
  OFFER_TYPES,
  DISCOUNT_AND_REWARD_TYPES,
  ITEM_CONDITION_TYPES,
} from '../../constants/offers'

const ADD_LOYALTY_CARD = 'Please add your loyalty card to proceed.'

/*
 * OFFER TYPES
 */
export default angular
  .module('offersServicesModule', [])
  .service(
    'offersService',
    function offersService(
      $filter,
      $q,
      $rootScope,
      $state,
      Restangular,
      cartService,
      config,
      errorModalService,
      lang,
      unataCommService
    ) {
      const service = this

      /*
       * Language constants
       */
      const {
        itemConditionType,
        cartConditionType,
        rewardType,
        complete,
        reward,
      } = lang.values.offersContent
      const { spendMoreToQualify, addItemsToQualify, addMoreToQualify } = itemConditionType
      const {
        spendMoreToComplete,
        buy: buyLang,
        one: oneLang,
        all: allLang,
        any: anyLang,
      } = cartConditionType
      const { multipleItemsForReward, singleItemForReward } = rewardType
      const { clipThisCoupon, offerCompleted, offerTimes } = complete
      const { getOffEach, getEachFor, getFor, free: freeLang, off: offLang } = reward

      /*
       * OFFER LABELS
       */
      const cachePromiseById = {}
      service.cachedOffers = {}

      // should probably have cart broadcast on rootscope that it changed
      const localScope = $rootScope.$new()
      localScope.cartService = cartService

      // eslint-disable-next-line complexity
      const labelOffer = function (offer) {
        const { offer_progress } = offer
        const offerProgressCondition = offer_progress.condition

        // Do some math with the offer progress conditions
        const offerConditionType = offer.condition.type
        const offerCondition = offer.condition[offerConditionType]
        const offerProgressDifference =
          offerProgressCondition.max_value - offerProgressCondition.current_value

        // Show how many products need to be added to qualify for offer
        if (
          ITEM_CONDITION_TYPES.includes(offerProgressCondition.type) &&
          offerProgressDifference > 0
        ) {
          if (offerCondition?.min_spend) {
            const offerProgressDifference = (
              offerCondition.min_spend - offerProgressCondition.current_value
            ).toFixed(2)
            offer_progress.label = lang.template(spendMoreToQualify, {
              conditionDifference: offerProgressDifference,
            })
          } else {
            const { min_weight } = offerCondition
            const minWeightStr = min_weight ? `(${min_weight}${offerCondition.uom})` : ''
            const itemTitle = $filter('pluralize')('item', offerProgressDifference)
            offer_progress.label = lang.template(addMoreToQualify, {
              conditionDifference: offerProgressDifference,
              itemTitle,
              minWeightStr,
            })
          }
        }

        // Show how much needs to be spent to qualify for the offer
        if (offerProgressCondition.type === CONDITION_TYPES.CART && offerProgressDifference > 0) {
          const conditionDifference = offerProgressDifference.toFixed(2)
          offer_progress.label = lang.template(spendMoreToComplete, { conditionDifference })
        }

        // Detect collection missing condition data
        if (
          offerProgressCondition.type === CONDITION_TYPES.COLLECTION &&
          offerProgressCondition.current_value === 0
        ) {
          offer_progress.label = addItemsToQualify
        }

        // Check if offer condition has been met
        if (offerConditionType !== CONDITION_TYPES.NONE && offerProgressDifference <= 0) {
          const { reward } = offer_progress
          // Show how many reward products need to be added to redeem the offer
          const remaining = reward.max_value - reward.current_value

          offer_progress.label =
            remaining > 1
              ? lang.template(multipleItemsForReward, { rewardDifference: remaining })
              : singleItemForReward
        }

        // Label offers as completed, with an optional count if completed more than once
        if (offerConditionType === CONDITION_TYPES.NONE || offer_progress.completion_count > 0) {
          if (offer.offer_type === OFFER_TYPES.COUPON && offer.clipped === false) {
            offer_progress.label = clipThisCoupon
          } else {
            const count = offer_progress.completion_count
            const text = count > 1 ? `${count} ${offerTimes}` : ''
            offer_progress.label = lang.template(offerCompleted, { text })
          }
        }

        // Generate titles for product collections
        // We only need titles where the condition / reward involves products
        if (offerConditionType === CONDITION_TYPES.PRODUCT) {
          const { products } = offer.condition

          const endSentence =
            products.quantity === 1
              ? oneLang
              : products.all_products
              ? `${allLang} ${products.products.length}`
              : `${anyLang} ${products.quantity}`

          offerProgressCondition.collection_title = `${buyLang} ${endSentence}`
        }

        const offerRewardType = offer.reward.type
        if (offerRewardType === OFFER_TYPES.DISCOUNT) {
          const { discount } = offer.reward
          let value = $filter('currency')(discount.value)
          let str = ''

          if (discount.type === DISCOUNT_AND_REWARD_TYPES.AMOUNT) {
            str = getOffEach
          } else if (discount.type === DISCOUNT_AND_REWARD_TYPES.PERCENT) {
            value = `${Math.floor(discount.value)}%`
            str = getOffEach
          } else if (discount.type === DISCOUNT_AND_REWARD_TYPES.SET_PRICE) {
            str = getEachFor
          }

          offerProgressCondition.collection_title += lang.template(str, { value })
        } else if (offerRewardType === OFFER_TYPES.PRODUCT) {
          const { type, value, quantity } = offer.reward.products
          const progressReward = offer_progress.reward
          let str = ' '

          progressReward.collection_title = lang.template(getFor, { quantity })

          if (
            (type === DISCOUNT_AND_REWARD_TYPES.PERCENT && value === 100) ||
            (type === DISCOUNT_AND_REWARD_TYPES.SET_PRICE && value === 0)
          ) {
            str += freeLang
          } else if (type === DISCOUNT_AND_REWARD_TYPES.AMOUNT) {
            str += `${$filter('currency')(value)} ${offLang}`
          } else if (type === DISCOUNT_AND_REWARD_TYPES.PERCENT) {
            str += `${Math.floor(value)}% ${offLang}`
          } else if (type === DISCOUNT_AND_REWARD_TYPES.SET_PRICE) {
            str += $filter('currency')(value)
          }

          progressReward.collection_title += str
        }

        // If the offer isn't activated the quantities will be 0.
        if (
          offer.offer_type === OFFER_TYPES.CONTINUITY &&
          offer.continuity.offer_activated === false
        ) {
          offerProgressCondition.collection_title = config.continuityOffers.qualifyingProducts
        }

        // Apply overrides for the product collection titles, if available from the offer
        if (offer.cdn_collection_title) {
          offerProgressCondition.collection_title = offer.cdn_collection_title
        }

        if (offer.rwd_collection_title) {
          offer_progress.reward.collection_title = offer.rwd_collection_title
        }
      }

      // If we want to make sure something happens after we perform an update to the offer state,
      //  we can assign the promise that updates the state as a property inside this object
      //  and then perform whatever needs to happen after by resolving it after this promise by using .then or
      //  (REMINDER: If any of the promises in the chain should fail, the subsequent promises will not execute)
      service.updatingOfferStatePromise = {}

      service.updateClippedState = offer =>
        (service.updatingOfferStatePromise[offer.id] = Restangular.one('offers', offer.id)
          .customPUT({ clipped: offer.clipped })
          .then(response => {
            cartService.fetch()
            if (service.cachedOffers[offer.id]) {
              service.cachedOffers[offer.id].clipped = response.clipped
            }
            $rootScope.$broadcast('couponOfferClippedStateUpdated')
          })
          .catch(response => {
            offer.clipped = !offer.clipped
            const message = (response.data && response.data.message) || response.message

            if (
              message === ADD_LOYALTY_CARD &&
              config.hasLoyalty &&
              !config.disableLoyaltyWhenAccountless
            ) {
              errorModalService
                .open(message, lang.values.addLoyaltyCard, lang.values.close)
                .then(() => {
                  $state.go('main.account.loyalty')
                })
            } else {
              errorModalService.open(message, 'OK')
            }
          }))

      const getOffer = offerId => {
        if (service.cachedOffers[offerId]) {
          service.updateOffer(service.cachedOffers[offerId])
          return $q.resolve(service.cachedOffers[offerId])
        }
        if (cachePromiseById[offerId]) {
          return cachePromiseById[offerId]
        }
        cachePromiseById[offerId] = Restangular.one('offers', offerId)
          .get()
          .then(offer => {
            service.updateOffer(offer)
            return offer
          })

        return cachePromiseById[offerId]
      }

      service.getOffer = offerId => {
        // If the clipped state of the offer is being updated, only get the offer after that's done
        if (service.updatingOfferStatePromise[offerId]) {
          return service.updatingOfferStatePromise[offerId].then(() =>
            getOffer(offerId).finally(() => {
              cachePromiseById[offerId] = null
            })
          )
        }
        return getOffer(offerId).finally(() => {
          cachePromiseById[offerId] = null
        })
      }

      service.fetchContinuityOffers = () => {
        if (config.enableContinuityOffers) {
          return Restangular.all('offers').getList({
            type: 'continuity',
            include_inactionable: true,
          })
        }
        return $q.resolve(null)
      }

      service.fetchPersonalizedOffers = () => {
        return Restangular.all('offers').getList(config.offersAndCoupons.queries.offers)
      }

      // Function to update offer progress data for non-cart API offers
      const updateOfferProgress = offerToUpdate => {
        // Stub out offer progress for those that haven't been started
        offerToUpdate.offer_progress = {
          completion_count: 0,
        }

        const offerUpdateType = offerToUpdate.condition.type
        if ([...ITEM_CONDITION_TYPES, CONDITION_TYPES.CART].includes(offerUpdateType)) {
          offerToUpdate.offer_progress.condition = {
            type: offerUpdateType,
            current_value: 0,
            max_value:
              offerUpdateType === CONDITION_TYPES.CART
                ? offerToUpdate.condition.cart.min_spend
                : ITEM_CONDITION_TYPES.includes(offerUpdateType)
                ? offerToUpdate.condition[offerUpdateType].quantity
                : 0,
          }
        } else {
          // No condition
          offerToUpdate.offer_progress.condition = {
            type: null,
            current_value: 0,
            max_value: 0,
          }
        }

        if (offerToUpdate.reward.type !== DISCOUNT_AND_REWARD_TYPES.NONE) {
          offerToUpdate.offer_progress.reward = {
            type: offerToUpdate.reward.type,
            current_value: 0, // Don't really use these anyway as offer
            max_value: 0, // would need to be in cart at this point
          }
        } else {
          // No reward
          offerToUpdate.offer_progress.reward = {
            type: null,
            current_value: 0,
            max_value: 0,
          }
        }
      }

      // Update a single offer that is passed in
      service.updateOffer = function (offer) {
        const offerInCart = _.find(cartService.cart && cartService.cart.offer_progress, {
          id: offer.id,
        })

        if (offerInCart) {
          offer.offer_progress = {
            condition: offerInCart.condition,
            reward: offerInCart.reward,
            completion_count: offerInCart.completion_count,
          }

          service.cachedOffers[offerInCart.id] = offer
        } else {
          updateOfferProgress(offer)
          service.cachedOffers[offer.id] = offer
        }

        labelOffer(offer)
      }

      // Used to generate offer data for offerDetails and the offerTile. These contexts only have a partial offer object.
      // If user_cart_offer is true, the offer associated with the cart (if it exists) will be returned. Otherwise a call to
      // the api will be made
      service.getOfferData = function (id, use_cart_offer = true) {
        const cartOffer = cartService.getOfferProgress(id)
        const deferred = $q.defer()

        // If the offer doesn't yet exist in the cart, we're going to want to fetch
        // & build it directly.
        if (!cartOffer || !use_cart_offer) {
          service.getOffer(id).then(result => {
            const offerModel = {
              clipped: result.clipped,
              completion_count: result.offer_progress ? result.offer_progress.completion_count : 0,
              condition: result.offer_progress ? result.offer_progress.condition : {},
              id: result.id,
              offer: result,
              reward: result.offer_progress ? result.offer_progress.reward : {},
            }

            deferred.resolve(offerModel)
          })
        } else {
          deferred.resolve(cartOffer)
        }
        return deferred.promise
      }

      // Determine the users continutity offers
      // This function is also used to provide updated continuity offers info so this event may be fired at any time.
      service.sendContinuityOffersMessage = () => {
        service.fetchContinuityOffers().then(offers => {
          // Map the API response to a simplified object
          const continuityOffers = offers.map(o => ({
            offer_activated: o.continuity.offer_activated,
            name: o.name,
            start_date: o.start_date,
            end_date: o.end_date,
            id: o.id,
            ext_id: o.ext_id,
          }))

          const payload = {
            continuityOffers,
          }

          unataCommService.send({
            eventType: unataCommService.EventType.CONTINUTIYOFFERS,
            payload,
          })
        })
      }

      // Listening for a 'unata-get-continuity-offers' message from client
      unataCommService.listen(
        unataCommService.EventType.GETCONTINUTIYOFFERS,
        service.sendContinuityOffersMessage
      )

      // Determine the users offers
      service.sendOffersMessage = () => {
        localScope.$watch('cartService.lastUpdated', () => {
          // Map the API response to a simplified object
          const offers = cartService.cart.offer_progress.map(o => ({
            offer_type: o.offer.offer_type,
            clipped: o.clipped,
            description: o.offer.description,
            id: o.id,
            ext_id: o.offer.ext_id,
            start_date: o.offer.start_date,
            end_date: o.offer.end_date,
          }))

          const payload = {
            offers,
          }

          unataCommService.send({ eventType: unataCommService.EventType.OFFERS, payload })
        })
      }

      // Listening for a 'unata-get-offers' message from client
      unataCommService.listen(unataCommService.EventType.GETOFFERS, service.sendOffersMessage)

      service.sendPersonalizedOffersMessage = () => {
        service.fetchPersonalizedOffers().then(offers => {
          // Map the API response to a simplified object
          const personalizedOffers = offers.map(o => ({
            offerType: o.offer_type,
            clipped: o.clipped,
            description: o.description,
            id: o.id,
            ext_id: o.ext_id,
            start_date: o.start_date,
            end_date: o.end_date,
          }))

          const payload = {
            total: personalizedOffers.length,
            offers: personalizedOffers,
          }

          unataCommService.send({
            eventType: unataCommService.EventType.PERSONALIZEDOFFERS,
            payload,
          })
        })
      }

      unataCommService.listen(
        unataCommService.EventType.GETPERSONALIZEDOFFERS,
        service.sendPersonalizedOffersMessage
      )
    }
  )
