import angular from 'angular'
import template from './recipeCell.html'

export default angular
  .module('RecipeCellComponentModule', [])
  .directive('recipeCell', function (imageURLService) {
    return {
      bindToController: {
        recipe: '=',
        openDetailsInModal: '=?',
        breadcrumbs: '=?',
      },
      controllerAs: 'recipeCell',
      template,
      replace: true,
      controller($filter, $state, mobileService, recipeDetailsModal, savedRecipesService) {
        const vm = this

        vm.imageURLService = imageURLService
        vm.mobile = mobileService

        vm.tagLimit = 6
        vm.recipeTags = vm.recipe.tags && vm.recipe.tags.tags ? vm.recipe.tags.tags : []

        vm.isSaved = () =>
          savedRecipesService.savedRecipesList.some(savedRecipe => {
            return vm.recipe.id === savedRecipe.id
          })

        vm.toggleSaveRecipe = (recipeId, index) => {
          savedRecipesService
            .requireLogin()
            .then(() => {
              if (!vm.isSaved()) {
                savedRecipesService.saveRecipe(recipeId, vm.recipe, index)
              } else {
                savedRecipesService.removeSavedRecipe(recipeId, vm.recipe, index)
              }
            })
            .catch(() => null)
        }

        // Recipe Details can be a modal or a regular page
        vm.linkToRecipeDetails = (recipeId, index) => {
          if (vm.openDetailsInModal) {
            recipeDetailsModal.open(recipeId, index)
          } else {
            $state.go('main.recipes.details', {
              index,
              recipeId,
              name: $filter('slugify')(vm.recipe.name),
              // starting from 2nd element since the first element is just a link to the recipes shop view, not a category
              categoryIds: vm.breadcrumbs
                ? vm.breadcrumbs.slice(1).map(breadcrumb => breadcrumb.params.recipe_category_id)
                : null,
            })
          }
        }
      },
    }
  })
