import angular from 'angular'

import { IC_PLACEMENTS } from '@app/angular/constants/express'
import { LOYALTY_ONBOARDING_STEPS } from '@app/angular/constants/loyaltyOnboarding'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'

export default angular
  .module('LoyaltyOnboardingModalControllerModule', [])
  .controller(
    'LoyaltyOnboardingModalController',
    function (
      $scope,
      $state,
      $window,
      auth,
      config,
      context,
      errorMessage,
      express,
      hideRegistration,
      lang,
      loyaltyOnboardingStep,
      mobileService,
      registrationService,
      socialSignOnData,
      socialSignOnService,
      uibModalAriaLabelService,
      userAnalytics,
      utilService
    ) {
      $scope.config = config
      $scope.context = context
      $scope.mobile = mobileService
      $scope.LOYALTY_ONBOARDING_STEPS = LOYALTY_ONBOARDING_STEPS
      $scope.loyaltyOnboardingStep = loyaltyOnboardingStep
      $scope.socialSignOnData = socialSignOnData
      $scope.currentState = $state.current.name
      $scope.utilService = utilService

      if (loyaltyOnboardingStep === 'intro') {
        userAnalytics.logUserLoyaltyOnboardingStart()
      }

      $scope.setLoyaltyOnboardingStep = function (step) {
        if (registrationService.hasOptionalAuthService()) {
          return registrationService.open(step)
        }
        // Only track events when modal is opened for loyalty onboarding and not from home page login dropdown
        if (context === LOGIN_CONTEXTS.LOYALTY_ONBOARDING) {
          switch (step) {
            case LOYALTY_ONBOARDING_STEPS.REGISTER:
              userAnalytics.logUserRegistrationStart({
                loyalty_onboarding: true,
                source: $scope.loyaltyOnboardingStep,
              })
              break
            case LOYALTY_ONBOARDING_STEPS.LOGIN:
              userAnalytics.logUserLoginStart({
                loyalty_onboarding: true,
                source: $scope.loyaltyOnboardingStep,
              })
              break
            case LOYALTY_ONBOARDING_STEPS.FORGOT:
              userAnalytics.logUserLoyaltyOnboardingForgotPassword()
              break
            case LOYALTY_ONBOARDING_STEPS.THANKYOU:
              userAnalytics.logUserLoyaltyOnboardingThankyouStart()
              break
            default:
              break
          }
        }

        switch (step) {
          case LOYALTY_ONBOARDING_STEPS.REGISTER:
            uibModalAriaLabelService.setLabel(lang.values.register)
            break
          case LOYALTY_ONBOARDING_STEPS.LOGIN:
            uibModalAriaLabelService.setLabel(lang.values.login)
            break
          case LOYALTY_ONBOARDING_STEPS.FORGOT:
            uibModalAriaLabelService.setLabel(lang.values.forgotPassword)
            break
          default:
            break
        }

        $scope.loyaltyOnboardingStep = step
      }

      const checkForUserContext = () => {
        if (!auth.user.context || !auth.user.context.intent) {
          auth.setContext({ intent: config.defaultShoppingContext })
        }
      }

      $scope.doCallToAction = function ({ uiState, text }) {
        userAnalytics.logUserLoyaltyOnboardingThankyouCTAClick({ action_type: text })

        if ($state.current.name === uiState) {
          checkForUserContext()
          $scope.$close($scope.loyaltyOnboardingStep)
        } else {
          checkForUserContext()
          $state.go(uiState, null, { reload: true })
        }
      }

      $scope.goToSocialLinkFlow = () => socialSignOnService.goToSocialLinkFlow(socialSignOnData)

      $scope.onRegister = () => {
        $scope.setLoyaltyOnboardingStep(LOYALTY_ONBOARDING_STEPS.THANKYOU)
      }

      $scope.goToStore = function (url, platform) {
        userAnalytics.logUserLoyaltyOnboardingThankyouCTAClick({
          action_type: `${platform} download`,
        })
        $window.location.href = url
      }

      // If we prompt the user to login to continue to a page (see `registrationService.promptLogin`)...
      $scope.hideRegistration = hideRegistration // Hide links to the registration
      $scope.errorMessage = errorMessage // Show them a message to login to continue

      $scope.express = express
      $scope.IC_PLACEMENTS = IC_PLACEMENTS
    }
  )
