import _ from 'lodash'
import angular from 'angular'

import { CART_TYPES } from '@app/angular/constants/cart.js'
import { product as enterpriseProductService } from '@instacart/enterprise-services'

export default angular
  .module('PurchaseControllerModule', [])
  .controller(
    'PurchaseController',
    function (
      $scope,
      config,
      eComLightService,
      imageURLService,
      list,
      productDetailsModal,
      purchase
    ) {
      $scope.config = config
      $scope.purchase = purchase
      $scope.productDetailsModal = productDetailsModal
      $scope.imageURLService = imageURLService

      $scope.selectedItems = []
      $scope.selectedMap = {}

      _.each($scope.purchase.items, item => {
        $scope.selectedMap[item.store_product.id] = false
      })

      $scope.isProductDetailsModalDisabled = item => {
        const isItemOOS = item =>
          !enterpriseProductService.utils.isProductAvailable(item.store_product)
        const isUnavailable =
          !item.store_product.status || item.status === 'removed' || isItemOOS(item)
        const isReorderable =
          _.isNil(item.product_config) ||
          _.isNil(item.store_product.product_config) ||
          _.isNil(item.store_product.product_config.status) ||
          item.store_product.product_config.status
        return (
          isUnavailable ||
          !isReorderable ||
          (!eComLightService.inEComLightMode &&
            $scope.purchase?.ext_data?.source_cart_type === CART_TYPES.CATERING) ||
          (eComLightService.inEComLightMode &&
            $scope.purchase?.ext_data?.source_cart_type !== CART_TYPES.CATERING)
        )
      }

      $scope.selectItem = function (item) {
        const itemIndex = _.findIndex($scope.selectedItems, selectedItemsItem => {
          // eslint-disable-next-line eqeqeq
          return selectedItemsItem.store_product.id == item.store_product.id
        })

        // eslint-disable-next-line eqeqeq
        if (itemIndex != -1) {
          $scope.selectedItems.splice(itemIndex, 1)
          $scope.noneSelected = false
        } else {
          $scope.selectedItems.push(item)
        }
      }

      $scope.selectAll = function () {
        if ($scope.noneSelected) {
          _.each($scope.purchase.items, item => {
            const itemIndex = _.findIndex($scope.selectedItems, selectedItemsItem => {
              // eslint-disable-next-line eqeqeq
              return selectedItemsItem.store_product.id == item.store_product.id
            })

            // eslint-disable-next-line eqeqeq
            if (itemIndex == -1 && item.store_product.status) {
              $scope.selectedItems.push(item)
              $scope.selectedMap[item.store_product.id] = true
            }
          })
        } else {
          $scope.selectedItems.length = 0
          _.each($scope.purchase.items, item => {
            $scope.selectedMap[item.store_product.id] = false
          })
        }
      }

      $scope.loadList = list.loadListToCart

      $scope.loadAll = function () {
        list.loadListToCart($scope.purchase.items)
      }

      $scope.categorizedProducts = _.groupBy($scope.purchase.items, item => {
        return item.store_product.categories[0]
          ? item.store_product.categories[0].name
          : 'Uncategorized'
      })
    }
  )
