import angular from 'angular'
import template from './provinceSelect.html'

export default angular
  .module('ProvinceSelectComponentModule', [])
  .directive('provinceSelect', function (config) {
    return {
      restrict: 'AE',
      scope: {
        provinceIsRequired: '=',
        province: '=',
        inputIdAttr: '@?',
        disabled: '@?',
        placeholder: '@?',
        errorElementId: '@?',
      },
      replace: true,
      template: template,
      controller: function ($scope, config, lang, values) {
        $scope.config = config
        $scope.lang = lang
        $scope.provinces = values[config.provinceOptions]

        // Set province to default if empty
        if (!$scope.province) {
          $scope.province = config.defaultProvince
        }
      },
    }
  })
