import angular from 'angular'
import template from './modifyQuantity.html'

export default angular
  .module('ModifyQuantityComponentModule', [])
  .directive('modifyQuantity', function () {
    return {
      scope: {
        item: '=', // {object} The product item
        incrementFn: '&', // {function} Callback for when the + button on the product is clicked
        decrementFn: '&', // {function} Callback for when the - button on the product is clicked
        onUpdate: '&', // {function} Callback for when the weight is changed via the dropdown
        onZeroFn: '&', // {function} Callback for when the 0lbs weight option is selected
        includeZero: '=?', // {boolean} Whether to include the 0lbs option on the weight dropdown
        analyticsName: '@', // {string} The analytics tracking name
        secondaryStyle: '<', // {boolean} Style the modify quanity component differently?
        disableInput: '<?', // {boolean} Disable the input
      },
      template,
      controller($scope, $element, $attrs, orderWeightOptionsService, lang) {
        $scope.lang = lang
        $scope.orderWeightOptionsService = orderWeightOptionsService
        $scope.maxItemsInCart = $scope.item.store_product?.ext_data?.max_in_cart || null

        $scope.shouldDisableButton = () => {
          return (
            $scope.disableInput ||
            $scope.item.quantity === $scope.item.ordering_parameters?.max ||
            $scope.item.quantity >= $scope.item.store_product?.ext_data?.max_in_cart
          )
        }

        // For QA regression and automation, need unique IDs between full and side cart
        $scope.context = $scope.analyticsName === 'side cart product' ? 'sidecart' : 'fullcart'

        // `store_product` needs to be set for when modify quantity is used via Modify Order Add-On feature
        // this feature necessitates this property because it appears in the PDP, which does not have `store_product`
        if ($scope.secondaryStyle) {
          $scope.item.store_product = $scope.item
        }

        /**
         * This is called when the modify quantity is changed - via the drop down
         *
         * @param {string} originalQuantity   - The string of the number selected
         */
        $scope.onUpdateValue = originalQuantity => {
          /**
           * onUpdate attribute called with two parameters (these will be recognised in the template)
           */
          $scope.onUpdate({
            $originalQuantity: parseFloat(originalQuantity),
            $onZeroFn: $scope.onZeroFn,
          })
        }
      },
    }
  })
