import _ from 'lodash'
import angular from 'angular'
import { captureException } from '@sentry/browser'
import { user } from '@instacart/enterprise-services'
import { USER_ACTIONS, CONTEXT_ACTIONS } from '@app/angular/constants/events'

export default angular
  .module('AddressListServicesModule', [])
  .service(
    'addressList',
    function addressList(
      $rootScope,
      Restangular,
      addressValidationService,
      config,
      localStorageService,
      ngPromise,
      profileService
    ) {
      const service = this

      service.addressItems = []
      service.icAddressItems = []
      const ADDRESSES_ENDPOINT = 'user_addresses'
      const validateAddressKeyPrefix = 'validated-address-id-'
      const validateAddressExpiry = 1000 * 30 * 60 // 30 minutes
      const getAddresses = ngPromise.wrapper(user.api.userAddresses.getAll)
      const updateAddress = ngPromise.wrapper(user.api.userAddresses.update)
      const createAddress = ngPromise.wrapper(user.api.userAddresses.create)
      const removeAddress = ngPromise.wrapper(user.api.userAddresses.remove)
      const fetchAddressActions = [
        USER_ACTIONS.USER_UPDATED,
        USER_ACTIONS.USER_LOGGED_IN,
        CONTEXT_ACTIONS.CONTEXT_UPDATED,
      ]

      service.fetchAddresses = () => {
        // piggy-back on this call to also fetch any addresses from the user's Instacart profile
        service.fetchInstacartAddresses()

        service.fetchAddressPromise = getAddresses().then(response => {
          return profileService
            .fetchProfile()
            .then(profile => {
              service.addressItems = response.items.map(address => {
                address.phone_number = address.phone_number || profile[profile.primary_number_key]
                return address
              })
              return service.addressItems
            })
            .catch(e => {
              captureException(e)
            })
        })

        return service.fetchAddressPromise
      }

      /**
       * Fetch any addresses in user's Instacart profile. If a user does not have an
       * Instacart profile this will return an empty array
       */
      service.fetchInstacartAddresses = () => {
        // This feature is disabled
        if (!config.icImportAddresses) {
          return Promise.resolve([])
        }

        service.fetchInstacartAddressesPromise = Restangular.all(`${ADDRESSES_ENDPOINT}/instacart`)
          .getList()
          .then(response => {
            service.icAddressItems = response.plain()
            return service.icAddressItems
          })
          .catch(() => []) // Ignore any errors
        return service.fetchInstacartAddressesPromise
      }

      /**
       * Imports a list of addresses in user's delivery address book
       */
      service.batchImportInstacartAddresses = addresses =>
        Restangular.all(`${ADDRESSES_ENDPOINT}/batch`)
          .customPOST(addresses)
          .then(addresses => {
            const newAddresses = addresses.plain()
            if (newAddresses) {
              // save addresses fetched from Instacart in user's delivery address book
              service.addressItems = [...service.addressItems, ...newAddresses.items]
            }
            return addresses
          })

      service.getAddresses = () => {
        return service.fetchAddressPromise.then(() => {
          return service.addressItems
        })
      }

      service.getAddressFromId = addressId => _.find(service.addressItems, o => o.id === addressId)

      const updateItemInList = address => {
        const updatedAddress = _.omit(address, ['session_token'])
        const currentAddress = service.getAddressFromId(address.id)
        if (currentAddress) {
          angular.extend(currentAddress, updatedAddress)
        } else {
          service.addressItems.unshift(updatedAddress)
        }
        return address
      }

      service.validateAddress = address => {
        return addressValidationService.validateAddress(address).then(response => {
          const recommendedAddresses = addressValidationService.createRecommendationsList(
            response.suggestions,
            address
          )

          if ((response.is_valid && recommendedAddresses.length > 0) || !response.is_valid) {
            return addressValidationService
              .openModal({
                original: address,
                recommended: recommendedAddresses,
                isValid: response.is_valid,
              })
              .then(modalResult => {
                return angular.extend(address, modalResult.delivery_address)
              })
          }
          // address is valid with no recommendation, update user
          return address
        })
      }

      service.validateAddressAndSaveIfChanged = address => {
        const initialAddress = angular.copy(address)
        return service.validateAddress(address).then(address => {
          if (addressValidationService.areAddressesSame(initialAddress, address)) {
            return address
          }
          return service.saveAddress(address)
        })
      }

      service.saveAddress = address => {
        if (address.id) {
          return updateAddress(address.id, address).then(response => {
            // If addresses are sourced from IC, they are immutable.
            // Updates will always result in a new ID.
            // Remove the old one.
            if (response.id !== address.id) {
              service.addressItems = service.addressItems.filter(addr => addr.id !== address.id)
            }
            return updateItemInList(response)
          })
        }
        return createAddress(address).then(addressResp => {
          service.addressItems.unshift(addressResp)
          return addressResp
        })
      }

      service.deleteAddress = addressId => {
        return removeAddress(addressId).then(response => {
          const index = _.findIndex(service.addressItems, o => o.id === addressId)
          if (index > -1) {
            service.addressItems.splice(index, 1)
          }
          return response
        })
      }

      service.isDeliveryAddressComplete = address => {
        return (
          address.first_name &&
          address.last_name &&
          address.address1 &&
          address.city &&
          address.province &&
          address.postal_code &&
          (address.phone_number || !config.collectPhoneNumbersDuringRegistration)
        )
      }

      service.markAddressValidated = addressId => {
        localStorageService.deprecatedSet(
          `${validateAddressKeyPrefix}${addressId}`,
          new Date().valueOf()
        )
      }

      service.unMarkAddressValidated = addressId => {
        localStorageService.deprecatedRemove(`${validateAddressKeyPrefix}${addressId}`)
      }

      service.checkAddressValidated = addressId => {
        const timestamp = localStorageService.deprecatedGet(
          `${validateAddressKeyPrefix}${addressId}`
        )
        return !!timestamp && new Date().valueOf() - timestamp < validateAddressExpiry
      }

      service.getDefault = () => {
        return service.addressItems[0]
      }

      service.getAddressRegionString = address => {
        if (!address) {
          return null
        }

        return [address.city, address.province].filter(value => !!value).join(', ')
      }

      service.hasAddressItems = () => service.addressItems && service.addressItems.length > 0
      service.hasIcAddressItems = () => service.icAddressItems && service.icAddressItems.length > 0

      $rootScope.$on('auth', (_event, action) => {
        if (fetchAddressActions.includes(action)) {
          service.fetchAddresses()
        }
      })
    }
  )
