import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { EVENTS } from '@app/angular/services/modules/analytics/analyticsConstants'
import { SHOPPING_SELECTOR_STAGES } from '@app/angular/constants/shoppingSelectorStages'
import parentShoppingSelectorTemplate from './views/parentShoppingSelector.html'

export default angular
  .module('ShoppingSelectorServiceModule', [])

  // Service to provide access to launching the parent modal where the user select his or her
  // shopping context, store and timeslot.

  .service(
    'shoppingSelectorService',
    function shoppingSelectorService(
      $rootScope,
      $state,
      $uibModal,
      analytics,
      auth,
      catalogCategoriesService,
      config,
      eComLightService,
      lang,
      mobileService,
      modalOptions,
      shoppingContextService,
      uibModalAriaLabelService,
      unataCommService
    ) {
      const service = this
      let suppressAnimation = false
      // If user closes the modal and there is no context set, then set it to SHOPPING_MODES.INSTORE_LIST.

      let initialContext
      let initialStore

      // Add listeners for UnataComm to trigger launching the context selector modal
      // to one of the following targets screens: shoppingContext, storeSelector, confirmation
      unataCommService.listen(unataCommService.EventType.OPENCONTEXTSELECTOR, event => {
        const payload = event.data.payload && event.data.payload.shoppingSelectorTarget
        const targetScreen = payload || SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT
        if (
          [
            SHOPPING_SELECTOR_STAGES.CONFIRMATION,
            SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT,
            SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
          ].includes(targetScreen)
        ) {
          service.open(targetScreen)
        }
      })

      service.forceContextIntentSet = function () {
        if (!auth.user.context || !auth.user.context.intent) {
          auth.setIntent(config.defaultShoppingContext)
          if (config.defaultShoppingContext === SHOPPING_MODES.CATERING) {
            shoppingContextService.setSelectedFulfillmentType(SHOPPING_MODES.CATERING)
          }
        } else if (
          auth.user.context &&
          auth.user.context.intent === SHOPPING_MODES.DELIVERY &&
          !auth.user.context.deliveryPostalCode
        ) {
          service.open(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
        }
      }

      function triggerOpenModal(shoppingSelectorTarget, errorMessage) {
        // We should only ever have one shoppingSelectorService modal at a time
        if (service.showing) {
          return
        }

        // When modifying an IC order we should not allow this modal to open to any view other than timeslots
        const selectorTargets = [
          SHOPPING_SELECTOR_STAGES.CONFIRMATION,
          SHOPPING_SELECTOR_STAGES.TIMESLOT,
        ]
        if (
          $rootScope.modifyingInstacartOrder &&
          !selectorTargets.includes(shoppingSelectorTarget)
        ) {
          return
        }

        initialContext = auth.getContext().intent
        initialStore = auth.getStore()
        /*
        Disable keyboard access (escape key and stuff) when:
        1. Enfore no close on store selector via config
        2. Or just in delivery mode and does not have valid deliviery postal code
        */
        const allowDeliveryModalClose =
          service.isContextIntentInValidState() ||
          !(
            config.enableMandatoryStoreSelection &&
            auth.user.context.intent !== SHOPPING_MODES.DELIVERY &&
            shoppingSelectorTarget === SHOPPING_SELECTOR_STAGES.STORE_SELECTOR
          )

        const modalInstance = $uibModal.open({
          template: parentShoppingSelectorTemplate,
          controller: 'ParentShoppingSelectorController',
          animation: !suppressAnimation,
          keyboard: allowDeliveryModalClose,
          backdrop: 'static',
          resolve: {
            ariaLabel: () => {
              uibModalAriaLabelService.setLabel(config.shoppingSelector[initialContext]?.title)
            },
            shoppingSelectorTarget() {
              return shoppingSelectorTarget
            },
            initialSelectorErrorMessage() {
              return errorMessage
            },
          },
        })

        service.showing = shoppingSelectorTarget
        service.closeModal = modalInstance.close

        suppressAnimation = false // Reset animation suppression

        return modalInstance.result.finally(() => {
          service.showing = false
          service.forceContextIntentSet()

          // Ignore change from null intent to actual intent
          const userChangedContext = initialContext && initialContext !== auth.getContext().intent
          const userChangedStore = initialStore !== auth.getStore()

          // Expose if user changed context/stores to refresh featured departments
          service.userChangedContextOrStore = userChangedContext || userChangedStore

          analytics.logSegmentEvent(EVENTS.CHECKOUT.SELECT_FULFILLMENT, {
            display_name: shoppingSelectorTarget,
          })

          // TODO: Move this category fetching logic elsewhere so we don't have to potentially repeat it
          //       in other places where we refresh the app and require category refreshes
          if (eComLightService.requiresRedirect) {
            eComLightService.requiresRedirect = false

            // Available categories may change, so refresh them.
            catalogCategoriesService.fetchCategories(true).then(() => {
              $state.go('main.home', {}, { reload: true })
            })
          } else if (userChangedContext || userChangedStore) {
            // Available categories may change, so refresh them.
            catalogCategoriesService.fetchCategories(true).then(() => {
              // Strip pagination from the query params platform-wide since when changing
              // the store/context we can no longer guarantee the current page will exist
              $state
                .transitionTo($state.current, { ...$state.params, page: null }, { location: true })
                .then(() => {
                  // This call can disrupt any modals or other UI components the user
                  // gets to before the fetching of categories is resolved
                  $rootScope.reloadState({ page: null }).then(() => {
                    $rootScope.$broadcast('contextSelectionComplete')
                  })
                })
            })
          } else {
            $rootScope.$broadcast('contextSelectionComplete')
          }

          modalOptions.reset()
        })
      }

      /**
       * Open the store selector in a modal dialog, optionally preventing the
       * closing of the dialog. Pass in the shoppingSelector Target and
       * optionally pass in an error message to display on initialization.
       * The options param can be optionally used to customize the the modal by
       * hiding the close and back buttons and the "select later" timeslot option
       *
       * @param {string} shoppingSelectorTarget The shopping selector target
       * @param {string} [errorMessage] optional error message
       * @param {object} [options] optional object that contains one or multiple attributes of
       *                           the modalOptions service
       *
       * @returns {Promise}
       */
      service.open = (shoppingSelectorTarget, errorMessage, options) => {
        modalOptions.setOptions(options)
        if (shoppingContextService.selectedFulfillmentType !== SHOPPING_MODES.CATERING) {
          shoppingContextService.setSelectedFulfillmentType(
            shoppingContextService.getIntentFromContext()
          )
        }

        if (
          shoppingSelectorTarget === SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT ||
          shoppingSelectorTarget === SHOPPING_SELECTOR_STAGES.STORE_SELECTOR
        ) {
          if (auth.user.store.is_b2b && auth.user.context.intent && mobileService.isMobile) {
            // If user is a b2b store and on mobile, force 'confirmation' to allow an exit from b2b mode
            return triggerOpenModal(SHOPPING_SELECTOR_STAGES.CONFIRMATION, errorMessage)
          }
          if (
            shoppingSelectorTarget === SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT &&
            !config.shoppingSelector.pickup.exists &&
            !config.shoppingSelector.delivery.exists &&
            !config.shoppingSelector.catering.exists
          ) {
            // If neither delivery nor pickup are supported, then select instore and skip to the store selector step.
            return auth.setIntent(SHOPPING_MODES.INSTORE_LIST).then(() => {
              return triggerOpenModal(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR, errorMessage)
            })
          }
          return triggerOpenModal(shoppingSelectorTarget, errorMessage)
        }
        return triggerOpenModal(shoppingSelectorTarget, errorMessage)
      }

      service.suppressAnimationOnNextOpen = () => {
        suppressAnimation = true
      }

      service.isShowing = function () {
        return !!service.showing
      }

      service.isContextIntentInValidState = function () {
        let isStateValid = true

        // If there is no context intent send user to shopping context selector

        if (!auth.user.context || !auth.user.context.intent) {
          isStateValid = false
        }

        // If user is set to delivery but doesn't have a postal code, clear context and go to shopping context selector.
        else if (
          auth.user.context &&
          auth.user.context.intent &&
          auth.user.context.intent === SHOPPING_MODES.DELIVERY &&
          !auth.user.context.deliveryPostalCode
        ) {
          isStateValid = false
        }

        return isStateValid
      }

      // Reset context intent and go to the shopping context selector

      service.resumeContextSwitch = function () {
        // if the only context is instore then we should not set the context to null
        let intent = null
        if (
          config.defaultShoppingContext === SHOPPING_MODES.CATERING &&
          auth.onlyCateringEnabled()
        ) {
          intent = SHOPPING_MODES.CATERING
        } else if (
          !config.shoppingSelector.delivery.exists &&
          !config.shoppingSelector.pickup.exists
        ) {
          intent = SHOPPING_MODES.INSTORE_LIST
        } else {
          intent = null
        }
        auth.setIntent(intent).then(() => {
          service.open(SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT)
        })
      }

      service.setContextForB2B = function () {
        auth.setContext({ intent: SHOPPING_MODES.PICKUP }).then(() => {
          service.open(SHOPPING_SELECTOR_STAGES.TIMESLOT)
        })
      }

      const shouldForceStoreDeliveryPostalCode = () => {
        const { context, store } = auth.user
        const { intent, deliveryPostalCode } = context

        return (
          config.forceSetDeliveryPostalCode &&
          intent === SHOPPING_MODES.DELIVERY &&
          !deliveryPostalCode &&
          store?.address?.postal_code
        )
      }

      const forceDeliveryPostalCode = () => {
        const storePostalCode = auth.user.store?.address?.postal_code.split('-')[0]
        auth.setContext({ deliveryPostalCode: storePostalCode })
      }

      service.homeStateContextHandler = function (skipModal) {
        if (!service.isContextIntentInValidState()) {
          if (shouldForceStoreDeliveryPostalCode()) return forceDeliveryPostalCode()

          if (config.showShoppingSelectorOnEnter && !skipModal) {
            service.resumeContextSwitch()
          } else {
            service.forceContextIntentSet()

            const skipToShopSelectorIntents = [SHOPPING_MODES.INSTORE_LIST, SHOPPING_MODES.PICKUP]
            if (
              config.showStoreSelectorOnEnter &&
              !skipModal &&
              skipToShopSelectorIntents.includes(auth.user.context.intent)
            ) {
              service.open(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
            }
          }
        }
      }
    }
  )
