import React from 'react'

/*
 * HOC - ngIgnore
 * This is a custom HOC to wrap all components implemented in angular.
 * It removes the text "ng-ignore" from all props.
 * This is required to work around the issue of prop names being treated as direcdtives.
 * Usage:
 *   Angular - <react-component ng-ignore-on-click={...} />
 *   React - <react-component onClick={...} />
 */

// eslint-disable-next-line no-unused-vars
const ngIgnore = BaseComponent => {
  // eslint-disable-next-line react/prefer-stateless-function
  class HOC extends React.Component {
    render() {
      const filteredProps = this.props

      const newProps = Object.keys(filteredProps).reduce((acc, key) => {
        let newKey = key.match(/^ngIgnore/i) ? key.replace(/^ngIgnore/i, '') : key
        newKey = newKey.charAt(0).toLowerCase() + newKey.slice(1)

        return {
          ...acc,
          [newKey]: filteredProps[key],
        }
      }, {})

      return <BaseComponent {...newProps} />
    }
  }

  return HOC
}

export default ngIgnore
