import angular from 'angular'

export default angular
  .module('ForgotPasswordButtonComponentModule', [])
  .component('forgotPasswordButton', {
    bindings: {
      setViewFn: '&',
    },
    controller(config, lang) {
      this.config = config
      this.lang = lang
    },
    template: `
      <button
        type="button"
        ng-if=":: !$ctrl.config.externalForgotPasswordHref"
        ng-click="$ctrl.setViewFn()"
        id="login-forgot" class="link" analytics-click="['login forgot password button', 'click']">
        {{::$ctrl.lang.values.forgotYourPassword}}
      </button>
      <a
        ng-if=":: $ctrl.config.externalForgotPasswordHref"
        href={{::$ctrl.config.externalForgotPasswordHref}}
        id="login-forgot" class="link" analytics-click="['login forgot password button', 'click']">
        {{::$ctrl.lang.values.forgotYourPassword}}
      </a>
    `,
  })
