import PropTypes from 'prop-types'

export const CARD_PROPS = PropTypes.shape({
  /** ExtId of the card */
  ext_id: PropTypes.string.isRequired,
  /** exp_month of the card */
  exp_month: PropTypes.number,
  /** exp_year of the card */
  exp_year: PropTypes.number,
  /** Has the card just been added? */
  is_new: PropTypes.bool,
  /** last_four_digits of the card */
  last_four_digits: PropTypes.string.isRequired,
  /** name of the card */
  name: PropTypes.string,
  /** provider of the card */
  provider: PropTypes.string,
  /** Whether the card is marked as a card to save */
  shouldSave: PropTypes.bool,
})
