import _ from 'lodash'
import angular from 'angular'
import template from './productPoints.html'

export default angular
  .module('ProductPointsComponentModule', [])
  .directive('productPoints', $filter => {
    return {
      scope: {
        product: '=',
      },
      template,
      controller($scope, config, lang) {
        $scope.config = config
        $scope.lang = lang

        $scope.hasOfferPoints = _.some($scope.product && $scope.product.offers, offer => {
          return offer.points !== null
        })

        // Select the first offer that has points associated with it.
        if ($scope.hasOfferPoints) {
          $scope.targetOffer = _.find($scope.product && $scope.product.offers, offer => {
            return offer.points !== null
          })
        }

        $scope.getFormatedDate = function (targetDate) {
          return targetDate
            ? lang.template(lang.values.saleEndDate, $filter('apiDate')(targetDate, 'MMM D'))
            : ''
        }
      },
    }
  })
