import angular from 'angular'
import dayjs from 'dayjs'

export default angular
  .module('FormattedBirthdayComponentModule', [])
  .directive('formattedBirthday', function () {
    return {
      restrict: 'A',
      require: 'ngModel',
      link(scope, el, attrs, modelCtrl) {
        modelCtrl.$parsers.push(birthday => {
          const format = attrs.format || 'MM/DD/YYYY'
          const obj = dayjs(birthday)
          let newBirthday = birthday

          if (obj.isValid()) {
            newBirthday = obj.format(format)
          }

          modelCtrl.$setViewValue(newBirthday)
          modelCtrl.$commitViewValue()
          if (newBirthday !== birthday) {
            modelCtrl.$render()
          }
          return birthday
        })
      },
    }
  })
