import angular from 'angular'

export default angular
  .module('MultpleBannerTabsComponentModule', [])
  .component('multipleBannerTabs', {
    bindings: {
      tabsConfig: '<',
      tabSelected: '&',
      currentValue: '<',
    },
    controller() {
      const vm = this
      vm.tabClicked = bannerSlug => {
        vm.tabSelected({ bannerSlug })
      }
    },
    template: `
      <div class="tabs">
        <multiple-banner-tabs-tab
          tab-title="$ctrl.tabsConfig.allStores"
          tab-banner-slug="''"
          tab-selected="$ctrl.tabClicked(bannerSlug)"
          current-value="$ctrl.currentValue"
        ></multiple-banner-tabs-tab>
        <multiple-banner-tabs-tab
          ng-repeat="banner in $ctrl.tabsConfig.banners"
          tab-title="banner.name"
          tab-banner-slug="banner.bannerSlug"
          tab-selected="$ctrl.tabClicked(bannerSlug)"
          current-value="$ctrl.currentValue"
        ></multiple-banner-tabs-tab>
      </div>
    `,
  })
  .component('multipleBannerTabsTab', {
    bindings: {
      tabTitle: '<',
      tabBannerSlug: '<',
      tabSelected: '&',
      currentValue: '<',
    },
    controller() {
      Object.assign(this, {
        tabClicked: () => this.tabSelected({ bannerSlug: this.tabBannerSlug }),
      })
    },
    template: `
      <div
        class="tab"
        role="button"
        ng-click="$ctrl.tabClicked()"
        ng-class="{active: $ctrl.currentValue === $ctrl.tabBannerSlug}"
      >
        <banner-image
          ng-if="$ctrl.tabBannerSlug"
          banner-slug="$ctrl.tabBannerSlug"
        ></banner-image>
        {{::$ctrl.tabTitle}}
      </div>
    `,
  })
