import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'

export default angular
  .module('sideCartServiceModule', [])
  .service(
    'sideCartService',
    function sideCartService($rootScope, $state, $timeout, auth, config, oneCartService) {
      const service = this

      service.openSideCart = () => ($rootScope.isSideCartOpen = true)
      service.closeSideCart = () => ($rootScope.isSideCartOpen = false)

      service.goToNextStep = () => {
        const { intent } = auth.getContext()
        const isCartMode = intent !== SHOPPING_MODES.INSTORE_LIST
        const state = isCartMode ? 'main.checkout.cart' : 'main.inStoreList.review'

        if (oneCartService.enabled() && isCartMode) {
          // Timeout because the event could be triggered by React, and we need a $rootScope refresh
          $timeout(service.openSideCart)
          return
        }

        // When OneCart is disabled or the user is in-store list mode, take the user to the review page
        $state.go(state)
      }
    }
  )
