import angular from 'angular'

export default angular
  .module('SubstitutionsComponentModule', [])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.account.substitutions', {
      url: '/substitutions',
      views: {
        'content@main.account': {
          template: require('./substitutions.html'),
          controller: 'SubstitutionsController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "substitutionsController", webpackPrefetch: true */ './substitutionsController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        redirect($state, $timeout, eComLightService) {
          if (eComLightService.inEComLightMode) {
            return $timeout(() => {
              $state.go('main.home')
            })
          }
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.substitutionPreferences)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.substitutionPreferences,
          },
        ],
      },
    })
  })
