import { WEGPAY } from '@app/angular/constants/paymentGateway'
import angular from 'angular'
import short from 'short-uuid'
import promiseable from '@app/utils/promiseable'
import { PAYMENT_POLYTYPES } from '@app/angular/constants/payments'
import {
  WEGPAY_ERROR_TYPES,
  WEGPAY_RETURN_CODES,
  WEGPAY_MASKS,
  WEGPAY_CARD_TYPE_CODES,
} from '@app/angular/constants/wegpay'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { importWegpayDependencies, showWegpayIframe } from './wegpayPaymentService'
import wegpayPaymentForm from './wegpayPaymentForm.html'

const shortUUid = short(short.constants.flickrBase58).generate

const TARGET_DOM_ID = 'divWegpay'
const CREDIT_CARD_TYPE = 'auth_capture'

export default angular
  .module('WegpayIntegrationControllerModule', [])
  .run($templateCache => $templateCache.put('wegpayPaymentForm.html', wegpayPaymentForm))
  .controller(
    'WegpayIntegrationController',
    function (
      $location,
      $q,
      $scope,
      $timeout,
      $window,
      Restangular,
      WegpayPayment,
      config,
      errorModalService,
      lang,
      utilService
    ) {
      const vm = this
      const services = {
        $location,
        $q,
        $scope,
        $timeout,
        $window,
        config,
        errorModalService,
        lang,
        Restangular,
        WegpayPayment,
        utilService,
      }

      /**
       * Initialize basic state
       */
      const { disableDelete } = config.wegpay
      let wegpayConfig = {}
      let wegpayCallback = null
      vm.cardName = null
      vm.cardZip = null
      vm.cardShouldSave = false
      vm.showAddInstrument = false
      vm.showAddInstrumentLoader = false
      vm.disableDelete = disableDelete
      vm.paymentInstruments = []
      vm.paymentInstrument = null

      /**
       * Define basic methods
       */
      const onShowCardForm = vm.onShowCardForm || $scope.$parent.onShowCardForm
      const notifyNewCreditCardFunction =
        vm.checkoutNewCreditCard || $scope.$parent.checkoutNewCreditCard
      const notifyNewCreditCard = val =>
        notifyNewCreditCardFunction && notifyNewCreditCardFunction(val)
      const notifyIsShowingAddCard = val =>
        $scope.toggleIsShowingAddCard && $scope.toggleIsShowingAddCard(val)
      vm.setCreditCard = () => $scope.doSave && $scope.doSave()

      const getDependencies = promiseable(async () => {
        wegpayConfig = await Restangular.one('payment')
          .one('config')
          .get({ service_type: $scope.paymentContext })

        const { iFrameJavaScript } = wegpayConfig || {}

        if (!iFrameJavaScript) {
          throw new Error('No iFrameJavaScript url was present - cannot load Wegpay!')
        }

        await importWegpayDependencies(
          utilService,
          wegpayConfig.iFrameJavaScript,
          WEGPAY.MIN_JQUERY_VERSION,
          WEGPAY.JQUERY_SRC
        )
      })

      /**
       * Triggers the process to load new cards
       */
      vm.loadCards = async () => {
        let hasInstruments = false

        try {
          vm.showCardLoader = true

          const { instruments, lastInstrument } = await _processPaymentInstruments(
            services,
            vm.paymentInstrument || $scope.payment?.[CREDIT_CARD_TYPE],
            !vm.paymentInstrument
          )

          if (instruments.length === 0) {
            vm.addPaymentInstrument()
          } else {
            hasInstruments = true
            instruments.forEach(instrument => vm.paymentInstruments.push(instrument))
            $timeout(vm.setCreditCard)
          }

          vm.paymentInstrument = lastInstrument
          return instruments
        } catch (e) {
          console.error('Could not load card list', e)
          vm.addPaymentInstrument()
        } finally {
          vm.showCardLoader = false
          if (hasInstruments) {
            $timeout(vm.setCreditCard)
          }
        }
      }

      /**
       * Triggers the process to add a card instrument from end to end
       */
      vm.addPaymentInstrument = async () => {
        let isError = false
        let payload = null

        // Indicate card is being added
        clearWegpayIframe()
        vm.showAddInstrument = true
        vm.showAddInstrumentLoader = true
        notifyIsShowingAddCard(true)

        try {
          // Initiate credit card adding process
          vm.auth_token = await putCreditCardType(services) // Indicate the credit card type "auth_capture"
          await getDependencies() // Ensure that the javascript assets are 100% loaded before continuing
          const iframePromise = showWegpayIframe(
            TARGET_DOM_ID,
            wegpayConfig,
            lang.values.wegpay,
            shortUUid(),
            shortUUid(),
            false,
            fn => (wegpayCallback = fn)
          )

          // Wait for iframe to be submitted
          if (onShowCardForm) {
            onShowCardForm()
          }
          vm.showAddInstrumentLoader = false
          const wegPayResponse = await iframePromise
          vm.showAddInstrumentLoader = true

          // Add new credit card
          payload = newCreditCardPayload({
            paymentInfo: wegPayResponse,
            name: vm.cardName,
            zip: vm.cardZip,
            saveOnFile: vm.cardShouldSave,
          })

          const newCreditCard = await postNewCreditCard(services, payload, vm.cardShouldSave)
          vm.paymentInstruments.push(newCreditCard)
          _sortPaymentListById(vm.paymentInstruments)
          vm.paymentInstrument = newCreditCard
          WegpayPayment.status.ready = true
          $timeout(vm.setCreditCard) // Collapse payment section to read only
        } catch (err) {
          // If non-validation, will collapse section, otherwise reopen after current digest
          isError = true
          const isValidationError = addCardFailed(services, err, payload)
          if (isValidationError) {
            $timeout(() => vm.addPaymentInstrument())
          }
        } finally {
          // Clean up of current adding session
          vm.showAddInstrument = false
          vm.showAddInstrumentLoader = false
          notifyNewCreditCard(!isError)
          notifyIsShowingAddCard(false)
          clearWegpayIframe()

          if (!isError) {
            vm.cardName = null
            vm.cardZip = null
            vm.cardShouldSave = false
          }
        }
      }

      /**
       * Called when the add new card button is clicked
       */
      vm.submit = () => {
        if (
          !vm.cardName ||
          !WEGPAY_MASKS.cardHolder.test(vm.cardName) ||
          !vm.cardZip ||
          !WEGPAY_MASKS.zipCode.test(vm.cardZip)
        ) {
          errorModalService.open(lang.values.wegpay.returnCodeLangMap.NAME_OR_ZIP, lang.values.ok)
          return
        }

        utilService.blurAndHideKeyboard(true)
        wegpayCallback() // Set dynamically above by Wegpay (see the addPaymentInstrument function)
      }

      /**
       * Called when the cancel new card button is clicked
       */
      vm.cancel = () => {
        vm.showAddInstrument = false
        vm.showAddInstrumentLoader = false
        notifyNewCreditCard(false)
        notifyIsShowingAddCard(false)
        clearWegpayIframe()
        utilService.blurAndHideKeyboard(true)
      }

      /**
       * Called when the delete link is clicked on a card
       */
      vm.deleteCard = async card => {
        try {
          // Show confirmation modal
          await errorModalService.open(
            `${lang.template(lang.values.deleteCardMessage, {
              cardName: card.name,
              cardNumber: card.last_four_digits,
            })}`,
            lang.values.confirm,
            lang.values.cancel,
            lang.values.deleteCard
          )

          // Issue delete to server
          vm.showCardLoader = true
          if (card.save_on_file !== false) {
            await Restangular.one('user/payment', card.id).remove({
              service_type: $scope.paymentContext,
            })
          }

          // Remove the card for the payment instruments list
          const cardIdx = vm.paymentInstruments.findIndex(
            instrument => instrument.ext_id === card.ext_id
          )

          if (cardIdx > -1) {
            vm.paymentInstruments.splice(cardIdx, 1)
          }

          const lastSelected = vm.paymentInstrument
          const deletedIsLast = lastSelected && lastSelected.ext_id === card.ext_id
          $timeout(() => {
            vm.showCardLoader = false
            vm.paymentInstrument = deletedIsLast ? vm.paymentInstruments[0] : lastSelected
          })
        } catch (e) {
          console.error('Could not delete card', e)
          $timeout(() => (vm.showCardLoader = false))
        }
      }

      /**
       * Watches "paymentInstrument" and updates "$scope.payment" so that the rest of the app knows
       * which payment method is selected
       */
      $scope.$watch(
        () => vm.paymentInstrument,
        () => {
          if ($scope.payment) {
            $scope.payment[CREDIT_CARD_TYPE] = {
              payment_instrument: vm.paymentInstrument,
            }
          }

          WegpayPayment.status.ready = !!vm.paymentInstrument
        }
      )

      /**
       * Trigger basic state updates
       */
      notifyIsShowingAddCard(false)
      getDependencies().catch(e => console.error('Could not preload dependencies', e))
      vm.loadCards()
    }
  )

/**
 * @public
 * Removes the Wegpay iframe from the DOM
 */
function clearWegpayIframe() {
  document.getElementById(TARGET_DOM_ID).innerHTML = ''
}

/**
 * @private
 * Retrieves a list of payment instruments from the server and processes them based on the passed correct state
 *
 * @param {Object} services                - An object of services (must include Restangular)
 * @param {Object} paymentInstrument       - The payment instrument last used (may not be original object!)
 * @param {boolean} initialCall            - Whether this is the initial call, or a subsequent call
 *
 * @return {Promise<{instruments, instrumentId}>}
 */
async function _processPaymentInstruments(
  { $scope, Restangular, WegpayPayment },
  paymentInstrument,
  initialCall
) {
  const out = { instruments: null, instrumentId: null }

  const response = await Restangular.one('user')
    .one('payment')
    .get({ payment_type: CREDIT_CARD_TYPE, service_type: $scope.paymentContext })

  const instruments = response[CREDIT_CARD_TYPE].payment_instruments || []

  Object.assign(out, { instruments })

  const firstInstrument = instruments[0]
  _sortPaymentListById(instruments)

  const { ext_id: lastExtId } = paymentInstrument || {}
  const stillExists = instruments.find(({ ext_id }) => ext_id === lastExtId)
  const notSavedToProfile = initialCall && paymentInstrument && !stillExists

  if (notSavedToProfile) {
    const { provider } = paymentInstrument
    paymentInstrument.provider = WEGPAY_CARD_TYPE_CODES[provider] || provider
    instruments.push(paymentInstrument)
    out.lastInstrument = paymentInstrument
  } else {
    out.lastInstrument = stillExists || firstInstrument
  }

  // Save paymentInstruments and the state of their `isNew` and `shouldSave` properties for further processing at Order Submit
  WegpayPayment.paymentInstruments = instruments

  return out
}

/**
 * @private
 * Sorts a payment list by id
 *
 * @param {Object[]} paymentList   - An array of payment objects

 *
 * @return undefined
 */
function _sortPaymentListById(paymentList) {
  paymentList.sort((a, b) => a.id - b.id)
}

/**
 * @public
 * Puts the credit card type to the server
 *
 * @param {Object<Restangular, $location>} services   - An object of services that must include Restangular and $location
 *
 * @return {Promise<String>}                          - transaction_setup_id
 */
async function putCreditCardType({ $scope, Restangular, $location }) {
  const response = await Restangular.one('cart')
    .one('payment')
    .customPUT({
      service_type: $scope.paymentContext,
      payment_type: CREDIT_CARD_TYPE,
      [CREDIT_CARD_TYPE]: {
        web_url: `${$location.$$protocol}://${$location.$$host}${
          $location.$$port ? `:${$location.$$port}` : ''
        }`,
      },
    })

  return response[CREDIT_CARD_TYPE].transaction_setup_id
}

/**
 * @public
 * Creates a payload object based on all the information, that can later be sent to the API
 *
 * @param {Object} paymentInfo             - An object to be sent for the payment info
 * @param {String} name                    - The card holder name
 * @param {String} zip                     - The zip associated with the card
 * @param {Boolean} saveOnFile             - True to ensure the card is saved on file
 *
 * @return {Object}                        - The structured payload sendable to the API
 */
function newCreditCardPayload({ paymentInfo, name, zip, saveOnFile }) {
  const {
    bin,
    expMonth,
    expYear,
    firstSix,
    id,
    invokeMerchantCallback,
    lastFour,
    message,
    orderId,
    paypageRegistrationId,
    reportGroup,
    response,
    responseTime,
    targetServer,
    type,
    vantivTxnId,
  } = paymentInfo

  return {
    ext_id: paypageRegistrationId,
    expiry_date: `20${expYear}-${expMonth}-01`, // Needs '20' at the front when using Wegpay 'numYears' option
    holder_name: name,
    last_four_digits: lastFour,
    polytype: PAYMENT_POLYTYPES.CREDIT_CARD,
    postal_code: zip,
    provider: type,
    ext_data: {
      save_on_file: saveOnFile,
      uiGenerated: {
        id,
        orderId,
      },
      wegpayBin: bin,
      // Wegpay sometimes returns bin as the string 'undef', which is truthy.
      bin: bin && bin !== 'undef' ? bin : firstSix,
      firstSix,
      invokeMerchantCallback,
      message,
      reportGroup,
      response,
      responseTime,
      targetServer,
      type,
      vantivTxnId,
    },
  }
}

/**
 * @public
 * Post the new credit card to the API
 *
 * @param {Object<Restangular>} services   - An object of services that must include Restangular
 * @param {Object} payload                 - An object to be sent for the payment info
 * @param {Boolean} saveOnFile             - Wether to save the card on file or not (used to know if a card id deleteable)
 *
 * @return {Promise<String>}                          - transaction_setup_id
 */
async function postNewCreditCard({ $scope, Restangular }, payload, saveOnFile) {
  const newCardResponse = await Restangular.one('user')
    .one('payment')
    .customPOST({
      ...payload,
      service_type: $scope.paymentContext,
    })

  const newCreditCard = newCardResponse.plain()
  const { provider } = newCreditCard
  newCreditCard.provider = WEGPAY_CARD_TYPE_CODES[provider] || provider
  newCreditCard.save_on_file = saveOnFile

  return newCreditCard
}

/**
 * @public
 * Called when a credit card fails to add
 *
 * @param {Object} services                           - An object of services that must include Restangular and $location
 * @param {Error} err                                 - The error that caused the failed card add
 * @param {Object | null} [payload]                   - OPTIONAL: A payload associated in causing the error
 *
 * @return {boolean}                                  - true if the error was only validation, false if it was unrecoverable
 */
function addCardFailed({ errorModalService, lang }, err, payload = null) {
  let errorType = null
  let message = lang.values.wegpay.defaultErrorMessage

  // eslint-disable-next-line no-restricted-globals, radix
  if (!isNaN(parseInt(err.response))) {
    errorType = WEGPAY_RETURN_CODES[err.response]

    if (errorType) {
      message =
        lang.values.wegpay.returnCodeLangMap[errorType] || lang.values.wegpay.defaultErrorMessage
    }
  }

  if (err.status === HTTP_ERRORS.TOO_MANY_REQUESTS) {
    message = lang.values.tooManyRetriesError
  }

  console.error('Add new card could not complete', err)
  if (payload) console.error('(Associated request CC payload)', payload)

  errorModalService.open(message, lang.values.ok)
  return errorType === WEGPAY_ERROR_TYPES.VALIDATION
}
