// A directive to autoFocus inputs expressly when they become visible in the UI
// Can be set directly: (auto-focus="true"), or set with an interpolated boolean
// value: (auto-focus="{{ someValue }}"). Important note, if {{ someVale }} is a
// string, it needs to be explicitly cast to a boolen.

import angular from 'angular'
import { DEVICE_TYPES } from '@app/angular/services/modules/mobile'

export default angular
  .module('AutoFocusModule', [])
  .directive('autoFocus', function ($timeout, mobileService) {
    return {
      link(scope, el, attrs) {
        // mobile safari does not play well with autoFocus
        if (mobileService.getMobileOs() !== DEVICE_TYPES.IOS) {
          const stopObserving = attrs.$observe('autoFocus', val => {
            if (val === 'true') {
              $timeout(() => {
                el[0].focus()
              }, 0)
            }
          })
          if (attrs.keepObserving) {
            scope.$on('$destroy', () => {
              stopObserving()
            })
          } else {
            stopObserving()
          }
        }
      },
    }
  })
