import angular from 'angular'
import isMobileApp from '@app/utils/isMobileApp'

import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'

export default angular
  .module('tagsServicesModule', [])

  .service(
    'tagsService',
    function tagsService(
      $rootScope,
      $state,
      auth,
      config,
      mobileService,
      oneCartService,
      registrationService
    ) {
      const service = this

      service.filterBarTags = []
      service.quickshopTags = []
      service.offerTags = []
      service.couponTags = []

      // Return a list of all product tags, removing those
      // that shouldn't be shown to the current user
      service.tags = function (reject) {
        const allTags = config.tags

        let hasOverrideIndex = false

        // Start off with all tags, optionally remove specified tags
        const filteredTags = allTags.filter(tag => {
          if (Array.isArray(reject)) {
            return reject.find(rejectValue => rejectValue === tag.value)
          }
          if (reject) {
            return tag.value !== reject.value
          }
          // Check if customTag has an index override
          if (!tag.index) {
            hasOverrideIndex = true
          }
          return tag
        })

        // Checks if filteredTags has an explicit index and rearranges array to reflect preferred order
        if (hasOverrideIndex) {
          return sortTags(filteredTags)
        }

        return filteredTags
      }

      // Returns an array of tags that have the 'requireRegistered' flag set to true
      service.requireRegisteredTags = function (activeFilters, allFilters) {
        let filterTag = {}
        const tags = []

        if (Array.isArray(activeFilters)) {
          activeFilters.forEach(tag => {
            filterTag = allFilters.find(filter => filter.id === tag) || {}
            if (filterTag.requireRegistered) {
              tags.push(tag)
            }
          })
        } else if (angular.isObject(activeFilters)) {
          angular.forEach(activeFilters, (value, tag) => {
            filterTag = allFilters.find(filter => filter.id === tag)
            if (value && filterTag.requireRegistered) {
              tags.push(tag)
            }
          })
        } else if (angular.isString(activeFilters)) {
          filterTag = allFilters.find(filter => filter === activeFilters) || {}
          if (filterTag.requireRegistered) {
            tags.push(activeFilters)
          }
        }

        return tags
      }

      // Opens a login modal. If user fails to login, removes any registerOnlyFilters. Supports callbacks
      service.checkRegistrationRequired = function (options) {
        const tagsRequiringRegistration = service.requireRegisteredTags(
          options.activeTags,
          options.allTags
        )
        if (auth.user.status === 'trial' && tagsRequiringRegistration.length > 0) {
          const removeRequiredTags = () => {
            if (Array.isArray(options.activeTags)) {
              angular.forEach(tagsRequiringRegistration, value => {
                const indexOfTag = options.activeTags.indexOf(value)
                if (indexOfTag > -1) {
                  options.activeTags.splice(indexOfTag, 1)
                }
              })
            } else if (angular.isObject(options.activeTags)) {
              angular.forEach(tagsRequiringRegistration, value => {
                // eslint-disable-next-line no-prototype-builtins
                if (options.activeTags.hasOwnProperty(value)) {
                  delete options.activeTags[value]
                }
              })
            }
          }
          // Opening login doesn't work properly on mobile navtive since it open it in an InAppBrowser
          // Which are closed when the states change.
          if (isMobileApp() && registrationService.hasOptionalAuthService) {
            removeRequiredTags()
            const promise = options.failure()
            return (
              promise &&
              promise.then(() => {
                return registrationService.open(
                  'login',
                  null,
                  null,
                  null,
                  null,
                  null,
                  LOGIN_CONTEXTS.TAG_LOGIN
                )
              })
            )
          }
          return registrationService
            .open('login', null, null, null, null, null, LOGIN_CONTEXTS.TAG_LOGIN)
            .then(
              () => {
                // If user logs in/registers, call success callback
                if (angular.isFunction(options.success)) {
                  // FRN-951: hack so we can tell the difference between successfully logging in and not needing to login/already logged in
                  options.success(true) // Login was required and was successful
                }
              },
              () => {
                // If user fails to login/register, uncheck all registerOnlyFilters, call failure callback
                $rootScope.isAppLoading = false
                removeRequiredTags()
                if (angular.isFunction(options.failure)) {
                  options.failure() // Login was required but failed
                }
              }
            )
        }
        if (angular.isFunction(options.success)) {
          // If user is logged in, call success callback right away
          options.success(false) // Login was either not required or the user is already logged in
        }
      }

      service.getSelectedFilters = (tags, brands) => {
        const selectedFilters = tags
          .map(filter => {
            const { name } = (filter && service.byVal(filter)) || {}
            return name || filter || null
          })
          .filter(filter => filter !== null)

        if (brands && brands.length > 0 && brands[0]) {
          Array.isArray(brands) ? selectedFilters.push(...brands) : selectedFilters.push(brands)
        }
        return selectedFilters.length > 0 ? selectedFilters.join(', ') : ''
      }

      // TODO should be in config
      // Helper functions to hide/show filters and whether or not to use facets
      const nonFacetedRoutes = [
        'main.shop.offers',
        'main.shop.coupons',
        'main.shop.flyer',
        'main.shop.flyer.category',
        'main.shop.recipes',
        'main.shop.recipes.category',
        'main.shop.recipes.saved',
        'main.shop.departments.featured',
        'main.shop.collections',
        'main.shop.collection',
        'main.shop.collection.category',
      ]

      service.useFacetedFilters = () =>
        (config.enableFacetedSearchFilters || config.enableICSearch) &&
        !nonFacetedRoutes.map(route => $state.is(route)).includes(true)
      service.hideFilters = () => $state.params.collection_id === 'specials'

      // tags filtered by location Eg return all quickshop tags
      const getTagsForLocation = location => {
        const tags = adjustTags(filterTags(config.tags, location), location)
        return sortTags(tags)
      }

      // tags not filtered by location. Location is used to get the overrides
      service.getTags = location => {
        const tags = adjustTags(config.tags, location)
        return sortTags(tags)
      }

      // filter tags by location.
      const filterTags = (tags, location) => tags.filter(tag => tag.locations?.[location])

      // sort tags by optional index attribute on the tags config
      // since not all tags can have an index we splice the tag into the expected
      // index
      const sortTags = tags => {
        tags.forEach((tag, index) => {
          if (tag.index) {
            tags.splice(tag.index, 0, tags.splice(index, 1)[0])
          }
        })
        return tags
      }

      // consolidate tag with location overrides
      const adjustTag = (tag, location) => {
        const { isMobile } = mobileService
        return cleanTag({
          ...tag,
          ...(tag.overrides && tag.overrides[location]),
          ...(isMobile && tag?.overrides?.[location]?.mobile),
        })
      }

      const adjustTags = (tags, location) => tags.map(tag => cleanTag(adjustTag(tag, location)))

      // remove uneeded objects from the tag
      const cleanTag = tag => {
        if (!tag) return
        const clonedTag = { ...tag }
        delete clonedTag.locations
        delete clonedTag.overrides
        delete clonedTag.mobile
        return clonedTag
      }

      // retrieve the tag config values by tag value/id
      service.byVal = (tagVal, location) => {
        const tag = service.tags().find(tag => tag.id === tagVal)
        if (!tag) return
        return adjustTag(tag, location)
      }

      // accepts tags and returns only those tags that have matching config tags
      // eg use on the pdp we do not want to show tags that arent configured
      service.getTagsMatchingConfigTags = (tagKeys, location) => {
        if (!tagKeys) return []
        return tagKeys.filter(tagKey => {
          const configTag = config.tags.find(ctag => ctag.id === tagKey)

          if (location) {
            return configTag?.locations?.[location] ?? configTag
          }
          return configTag
        })
      }

      // Some tags have dynamic visibility conditions, so we use this function
      // to check the conditions at time of use rather than service instantiation.
      service.filterDynamicTags = tags =>
        tags?.filter(tag => {
          if (tag.id === 'usa_snap_eligible') {
            return (
              config.ebt?.enabled &&
              oneCartService.enabled() &&
              auth.user?.store?.ebt_enabled_in_state &&
              auth.user?.status !== 'trial'
            )
          }

          return true
        })

      // Build the static filterBar and quickshop tags when this service instantiates
      ;(() => {
        service.filterBarTags = getTagsForLocation('filters')
        service.quickshopTags = getTagsForLocation('quickshop')
        service.offerTags = getTagsForLocation('offers')
        service.couponTags = getTagsForLocation('coupons')
      })()
    }
  )
