import angular from 'angular'
import { COLLECTION_CONSTANTS } from '@app/angular/constants/collections'

export default angular
  .module('collectionsServicesModule', [])
  .service('collectionsService', function collectionsService(Restangular) {
    /**
     * The collections endpoint does some filtering AFTER slicing the product list to the given limit.
     * This leads to scenarios where less than 5 products are returned from the collections endpoint.
     * Making changes in the BE to perform the filtering BEFORE slicing may lead to unexpected performance implications
     * and is too big of a change for now.
     *
     * So, temporarily, we'll request double the products from the collections endpoint (which should reduce the number of times that less than 5 products are returned)
     * and then slice to the limit on the FE.
     *
     * Once the BE collections code has been fixed to appropriately respect the given limit, we can remove this logic.
     *
     * ref: ENT-24463
     */
    const COLLECTION_LIMIT_MULTIPLIER = 2
    const service = this

    const cachedCollections = {}

    // Function to fetch a collection by ID - will return the cahced collection
    // if one already exists for the given identifier
    service.getCollection = (id, limit, refresh) => {
      const collectionIdentifier = `${id}_${limit}`
      if (cachedCollections[collectionIdentifier] && !refresh) {
        return cachedCollections[collectionIdentifier]
      }
      cachedCollections[collectionIdentifier] = Restangular.one('collections', id)
        .get({ limit: limit * COLLECTION_LIMIT_MULTIPLIER })
        .then(collectionData => {
          if (collectionData.items) {
            const items = collectionData.items.slice(0, limit)
            collectionData.items = items
          }
          return collectionData
        })
        .catch(() => [])
      return cachedCollections[collectionIdentifier]
    }

    // Function to fetch a set of collections from an arrary of collection ID's
    // Returns an array of collection objects
    service.getCollectionsFromSet = collectionsArray => {
      const collections = []
      const promises = collectionsArray.map(collectionID => {
        return new Promise(resolve => {
          service
            .getCollection(collectionID, COLLECTION_CONSTANTS.PRODUCT_LIMIT)
            .then(collection => {
              collections.push(collection)
              resolve(collection)
            })
        })
      })

      return Promise.all(promises)
    }
  })
