import angular from 'angular'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import { storeSupportsContext } from '@app/utils/storeSupportsContext'
import { multiShoppingContextsAvailableSelector } from '@app/services/shoppingContext/selectors'
import { getBannerConfig, hasBannerImage } from '@app/utils/banners'
import bannerSwitchingTabTemplate from '../../components/nav/views/bannerSwitchingTabTemplate.mobile.html'

export default angular
  .module('contextSelectorButtonServiceModule', [])
  .service(
    'contextSelectorButtonService',
    function (
      $rootScope,
      $state,
      $uibModal,
      analytics,
      auth,
      catalogCategoriesService,
      config,
      lang,
      modalOptions,
      reservationService,
      shoppingSelectorService,
      uibModalAriaLabelService
    ) {
      /**
       * This handles the button clicks within the context selector. This is a short term measure to support the new
       * react context-selector component.
       *
       * TODO: This should be refactored into the new selector modal enterprise service when that gets built. Context,
       * type and config should all be handled within the new service.
       *
       * @param {string} type - Button type clicked on within context selector
       *
       * @returns {undefined}
       */

      eventEmitter.removeAllListeners(EVENT_TYPES.CONTEXT_SELECTOR_BUTTON_CLICK)

      this.triageSelectorButtonClicks = type => {
        switch (type) {
          case 'icon':
            openShoppingSummary(config.defaultSelectorService.desktopHomeIcon)
            break
          case 'context':
            openShoppingSummary(config.defaultSelectorService.desktopHomeContext)
            break
          case 'store':
            if (auth.user.context.intent === 'delivery') {
              openShoppingSummary(config.defaultSelectorService.desktopHomeDelivery)
            } else {
              openShoppingSummary(config.defaultSelectorService.desktopHomeStore)
            }
            break
          case 'reservation':
            if (reservationService.displayReservation) {
              openShoppingSummary(config.defaultSelectorService.desktopHomeReservation)
            } else {
              handleReserveTimeslotClick()
            }
        }
      }

      const openShoppingSummary = shoppingSelectorRoute => {
        modalOptions.reset()
        shoppingSelectorService.open(shoppingSelectorRoute)
      }

      const handleReserveTimeslotClick = () => {
        modalOptions.reset()
        modalOptions.setOptions({ showBackButton: false })
        openTimeslotSelector()
      }

      const openTimeslotSelector = () => shoppingSelectorService.open('timeslot')

      const getBannerNameTitle = store_banner => {
        const { name } = config.multipleBanners.banners.find(
          banner => banner.bannerExtId === store_banner.ext_id
        )

        return name
      }

      const openBannerSwitchingModal = () => {
        $uibModal.open({
          template: bannerSwitchingTabTemplate,
          controller: function bannerSwitchingModalController(
            $ngRedux,
            $scope,
            auth,
            config,
            lang,
            shoppingSelectorService
          ) {
            $scope.auth = auth
            $scope.config = config
            $scope.lang = lang
            $scope.userStore = auth.user.store
            $scope.shoppingSelectorService = shoppingSelectorService
            $scope.currentContext = auth.user.context.intent
            $scope.storeSupportsContext = storeSupportsContext
            $scope.hasBannerImage = hasBannerImage()
            $scope.getBannerNameTitle = getBannerNameTitle
            $scope.selectedLoading = false

            const mapStateToService = state => ({
              multiShoppingContextsAvailable: multiShoppingContextsAvailableSelector(state),
            })
            $ngRedux.connect(mapStateToService)($scope)

            $scope.updateStore = store => {
              auth.update({ store_id: store.id }).then(() =>
                auth
                  .setContext({
                    storeNumber: store.id,
                    retailer_slug: store.store_banner?.key,
                    intent: auth.getContext().intent,
                  })
                  .then(() =>
                    // Available categories may change, so refresh them.
                    catalogCategoriesService
                      .fetchCategories(true)
                      .then(() =>
                        $state.go('main.home').then($scope.$close).then($rootScope.reloadState)
                      )
                  )
                  .then(() => auth.sendUserUpdatedStore(auth.user))
              )
            }

            $scope.selectAssociatedStore = store => {
              $scope.selectedLoading = true

              const banner = getBannerConfig(store.store_banner?.key)
              const isAlcoholBanner = Boolean(banner?.alcoholExclusive)

              const eventName = isAlcoholBanner
                ? 'store.click_alcohol_storefront'
                : 'store.click_grocery_storefront'

              analytics.logSegmentEvent(eventName, {})
              $scope.updateStore(store)
            }
          },
          resolve: {
            ariaLabel: () => {
              uibModalAriaLabelService.setLabel(lang.values.pleaseSelect)
            },
          },
        })
      }

      this.openMobileContextSelector = () => {
        config.multipleBanners.enableNonTabulatedView
          ? openBannerSwitchingModal()
          : shoppingSelectorService.open(config.defaultSelectorService.mobileNavTopBar)
      }

      // Enable the react header to open angular context selector modals
      // TODO: Move modal logic into context selector button react component
      // once the modals are available there.
      eventEmitter.on(EVENT_TYPES.CONTEXT_SELECTOR_BUTTON_CLICK, this.triageSelectorButtonClicks)
      eventEmitter.on(EVENT_TYPES.MOBILE_CONTEXT_SELECTOR, this.openMobileContextSelector)
    }
  )
