import angular from 'angular'
import {
  product as enterpriseProductService,
  shoppingList as shoppingListService,
} from '@instacart/enterprise-services'
import { FULFILLMENT_TYPES } from '@app/angular/constants/checkout'
import { UNIT_OF_MEASURE } from '@app/angular/constants/uom'
import { utils as timeslotUtils } from '@instacart/enterprise-timeslots'
import debounce from 'lodash/debounce'
import cloneDeep from 'lodash/cloneDeep'
import isEqual from 'lodash/isEqual'
import dayjs from 'dayjs'

import { DATE_OF_BIRTH_FORMAT } from '@app/angular/constants/alcoholValidation'

export default angular
  .module('OrderEditItemControllerModule', [])
  .controller(
    'OrderEditItemController',
    function (
      $filter,
      $rootScope,
      $scope,
      $state,
      Restangular,
      cartService,
      catalogCategoriesService,
      config,
      configurableProductService,
      errorModalService,
      imageURLService,
      lang,
      offersService,
      order,
      orderFeesService,
      savedOrderItemStash,
      setPageHeaderTitle
    ) {
      setPageHeaderTitle(
        `${lang.values.orderEditItemsFor} ${
          order.fulfillment_type === FULFILLMENT_TYPES.DELIVERY
            ? lang.values.delivery
            : lang.values.pickup
        }`
      )
      // We need to keep the original list to ensure that the order has actually been modified
      const originalOrderItems =
        savedOrderItemStash?.modifyOrderItems ||
        order.order_items.map(item => ({ ...item, comment: item.customer_comment }))

      /*
       * Initialize scope with info
       */
      Object.assign($scope, {
        catalogCategoriesService,
        config,
        configurableProductService,
        imageURLService,
        lang,
        order,
        mobileShowNotesAndSubs: false,
        currencyFilter: $filter('currency'),
        getMinCartSpend: shoppingListService.commonUtils.getMinimumCartSpend,
        substitutions: {
          allowAllSubstitutions: order.order_items.every(item => item.allow_substitutions),
        },
        orderTotals: {
          ...order.estimated_totals,
        },
        orderTotalLines: [],
        isCalculationRequestLoading: false,
        isCalculationRequestBuffered: false,
        isCalculationRequestDebounced: false,
        // Create a copy of order.order_items for modification
        modifyOrderItems: cloneDeep(originalOrderItems),
        modifyOfferProgress: order.offer_progress || [],
      })

      /**
       * Returns a quantity within the max and min paramaters of an item's quantity config.
       *
       * @param {object} item Item object
       * @param {number} quantity Quantity of item to clamp
       */
      $scope.clampedItemQuantity = (item, quantity) =>
        Math.min(
          Math.max(quantity, item.store_product?.ordering_parameters?.min || -Infinity),
          item.store_product?.ordering_parameters?.max || Infinity
        )

      /*
       * Merge appendedItems if provided by the Add Items page
       */
      if (savedOrderItemStash?.appendedItems?.length > 0) {
        $scope.hasAppendedItems = true

        savedOrderItemStash.appendedItems.forEach(newItem => {
          const existingItem = $scope.modifyOrderItems.find(
            existingItem => existingItem.store_product.id === newItem.store_product.id
          )

          if (existingItem) {
            const newQuantity = existingItem.quantity + newItem.quantity
            existingItem.quantity = $scope.clampedItemQuantity(newItem, newQuantity)
          } else {
            $scope.modifyOrderItems.push(
              convertNewItemToEditItem(newItem, $scope.modifyOrderItems.length)
            )
          }
        })

        savedOrderItemStash.appendedItems = null
      }

      /*
       * Begin main Edit Items page
       */
      let lastValidCalculateResponse = { order_items: [...$scope.modifyOrderItems] }

      $scope.updateCategorizedItems = () => {
        const productsWithAvailabilityStatus = $scope.modifyOrderItems
          .filter(item => item.store_product?.categories)
          .map(item => {
            // We want to only grey-out/cross-out substituted products if they are unavailable or completely removed from the order
            // (whether the item has simply been removed or if the entire quantity has been fully substituted)
            const isUnavailable = !item.store_product.status || item.status === 'removed'
            return { ...item, isUnavailable }
          })

        return catalogCategoriesService
          .getOrderedCategoriesAndProductsWithCategories(productsWithAvailabilityStatus)
          .then(({ orderedCategories, categorizedProducts }) => {
            $scope.orderedCategories = orderedCategories
            $scope.categorizedProducts = categorizedProducts
          })
      }

      /**
       * Returns true if the offer discount should be shown.
       *
       * @returns { boolean }
       */
      $scope.showOfferDiscount = () => {
        return (
          config.showOfferDiscountAndCreditsApplied ||
          parseInt($scope.orderTotals.offer_total, 10) !== 0
        )
      }

      /**
       * Returns true if the credit applied should be shown.
       *
       * @returns { boolean }
       */
      $scope.showCreditsApplied = () => {
        return (
          config.showOfferDiscountAndCreditsApplied ||
          parseInt($scope.orderTotals.credit_total, 10) !== 0
        )
      }

      /**
       * Returns whether estimated total is below minimum.
       *
       * @returns { boolean }
       */
      $scope.isTotalBelowMin = () => {
        const cartMinimum = shoppingListService.commonUtils.getMinimumCartSpend()
        return cartMinimum && $scope.orderTotals.product_total < cartMinimum
      }

      /**
       * Sets totals and total lines for display.
       *
       * @param {object} totals Totals for the order
       */
      $scope.setEstimatedOrderTotals = totals => {
        $scope.orderTotals = totals
        let setOrderTotalsLines = []

        const fees = [() => totals?.fees, () => totals]

        const addOrderTotalLineValue = (label, value, shouldAdd = true, additionalProps = {}) => {
          if (value && shouldAdd) {
            setOrderTotalsLines.push({ type: 'value', label, value, ...additionalProps })
          }
        }

        addOrderTotalLineValue(lang.values.itemTotal, totals.product_total)

        if (!$scope.isTotalBelowMin()) {
          addOrderTotalLineValue(
            lang.values.creditsApplied,
            totals.credit_total,
            $scope.showCreditsApplied()
          )

          addOrderTotalLineValue(
            lang.values.offerDiscount,
            -totals.offer_total,
            $scope.showOfferDiscount()
          )

          addOrderTotalLineValue(lang.values.tip, totals.tip_total)
          addOrderTotalLineValue(lang.values.tax, totals.tax_total)

          const formattedFees = orderFeesService.formattedFees(fees, order.fulfillment_type)

          if (formattedFees.length > 0) {
            setOrderTotalsLines.push({
              type: 'divider',
            })

            setOrderTotalsLines = setOrderTotalsLines.concat(formattedFees)

            setOrderTotalsLines.push({
              type: 'divider',
            })
          }

          addOrderTotalLineValue(lang.values.total, totals.total)
        }

        $scope.orderTotalLines = setOrderTotalsLines
      }

      $scope.setEstimatedOrderTotals(order.estimated_totals)

      /**
       * Gets line number of an item (with support for previous key name).
       *
       * @param {object} item Item to get line number from
       */
      $scope.getItemLineNumber = item => item.line_number || item.lineNumber

      /**
       * Updates an item and optionally redraws the view.
       *
       * @param {number} lineNumber Line number of item to update
       * @param {object} valuesToUpdate Values to update on the item
       * @param {boolean} [updateView=true] Whether to redraw the view
       */
      $scope.updateModifyItem = (lineNumber, valuesToUpdate, updateView = true) => {
        const foundItem = $scope.modifyOrderItems.find(
          modifyItem => $scope.getItemLineNumber(modifyItem) === lineNumber
        )

        if (foundItem) Object.assign(foundItem, valuesToUpdate)

        if (updateView) $scope.updateCategorizedItems()
      }

      /**
       * Updates the commnt of an item.
       *
       * @param {object} item Item object with updated comment
       */
      $scope.updateItemComment = item => {
        $scope.updateModifyItem($scope.getItemLineNumber(item), {
          customer_comment: item.comment,

          // For display purposes.
          comment: item.comment,
        })
      }

      /**
       * Toggles whether substitutions are allowed. If substitutions are disabled in a config for a the item,
       * the value will not change. Also updates the "Allow All Substitutions" toggle to match.
       *
       * @param {object} item Item object to toggle
       */
      $scope.toggleAllowSubstitutions = item => {
        if ($scope.isAllowSubstitutionsDisabled(item)) return

        $scope.updateModifyItem($scope.getItemLineNumber(item), {
          allow_substitutions: item.allow_substitutions,
        })

        $scope.substitutions.allowAllSubstitutions = $scope.modifyOrderItems.every(
          item => item.allow_substitutions || $scope.isAllowSubstitutionsDisabled(item)
        )
      }

      /**
       * Toggles the "Allow All Substitutions" checkbox and changes each item to match (unless substitutions
       * are disabled in a config for a the item).
       */
      $scope.toggleAllowAllSubstitutions = () => {
        $scope.modifyOrderItems.forEach(item => {
          if (!$scope.isAllowSubstitutionsDisabled(item)) {
            item.allow_substitutions = $scope.substitutions.allowAllSubstitutions
          }
        })

        $scope.updateCategorizedItems()
      }

      /**
       * Removes an item from the list.
       *
       * @param {object} item Item object to remove
       */
      $scope.removeItem = item => {
        const foundItemIndex = $scope.modifyOrderItems.findIndex(
          modifyItem => $scope.getItemLineNumber(modifyItem) === $scope.getItemLineNumber(item)
        )

        $scope.modifyOrderItems.splice(foundItemIndex, 1)

        $scope.updateCategorizedItems()
        $scope.handleCalculateOrderTotals()
      }

      /**
       * Shows confirm delete action for the item if quantity is below minimum
       *
       * @param {object} item - The product item being updated
       * @param {object} [relativeQuantityChange=0] - Relative quantity difference from current item quantity
       *
       * @returns {boolean} Whether the delete confirmation was shown
       */
      $scope.removeItemIfMin = (item, relativeQuantityChange = 0) => {
        if (
          item.quantity + relativeQuantityChange <
          (item.store_product?.ordering_parameters?.min || 0)
        ) {
          item.showDeleteConfirm = true
          return true
        }

        return false
      }

      /**
       * Increments the quantity of an object.
       *
       * @param {object} item Item object to increment
       */
      $scope.incrementItem = item => {
        if ($scope.removeItemIfMin(item, 1)) return
        $scope.updateModifyItem($scope.getItemLineNumber(item), {
          quantity: $scope.clampedItemQuantity(item, item.quantity + 1),
        })

        $scope.handleCalculateOrderTotals()
      }

      /**
       * Decrements the quantity of an object, showing the "remove" prompt if the quantity is lower
       * than the min quantity.
       *
       * @param {object} item Item object to increment
       */
      $scope.decrementItem = item => {
        if ($scope.removeItemIfMin(item, -1)) return
        $scope.updateModifyItem($scope.getItemLineNumber(item), {
          quantity: $scope.clampedItemQuantity(item, item.quantity - 1),
        })

        $scope.handleCalculateOrderTotals()
      }

      /**
       * Triggered when the quantity is updated
       *
       * @param {object} item - The product item being updated
       */
      $scope.updateItemQuantity = item => {
        if ($scope.removeItemIfMin(item)) return
        $scope.updateModifyItem($scope.getItemLineNumber(item), {
          quantity: $scope.clampedItemQuantity(item, item.quantity),
        })

        $scope.handleCalculateOrderTotals()
      }

      /**
       * Hides the delete confirmation prompt for an item.
       *
       * @param {object} item Item object
       */
      $scope.cancelRemoveItem = item => {
        // Redraw the item. showDeleteConfirm is falsey by default, so this will hide the confirmation.
        $scope.updateModifyItem($scope.getItemLineNumber(item))
      }

      // Used to ensure ng-repeat's `track by` works with one-time binding
      $scope.generateUniqueItemId = item =>
        `${item.store_product.id}-${configurableProductService.generateConfigIdString(item)}`

      $scope.timeslotLabelFn = () =>
        timeslotUtils.getMainLabel($scope.order, {
          verboseLevel: 1,
          format: $scope.config.apiDateFormat,
        })

      /**
       * Returns whether an item's substitutions are disabled based on config.
       *
       * @param {object} item Item object
       */
      $scope.isAllowSubstitutionsDisabled = item => {
        return !$scope.config.enableAllowSubstitutionsForConfigurableItems && item.product_config
      }

      /**
       * Returns whether an item is currently out of stock.
       *
       * @param {object} item Item object
       */
      $scope.isItemCurrentlyOutOfStock = item => {
        return !enterpriseProductService.utils.isProductAvailable(item.store_product)
      }

      /**
       * Sets whether the UI for editing notes and substitions is enabled on mobile.
       *
       * @param {boolean} isShown Whether to show the UI for the edit notes and subs mode.
       */
      $scope.setMobileShowNotesAndSubs = isShown => {
        $scope.mobileShowNotesAndSubs = isShown
      }

      /**
       * Calculate totals and update order item sub totals
       */
      $scope.calculateOrderTotals = () => {
        // Buffer the request to ensure that multiple calculate calls aren't made simultaneously
        if ($scope.isCalculationRequestLoading) {
          $scope.isCalculationRequestBuffered = true
          return
        }

        $scope.isCalculationRequestDebounced = false
        $scope.isCalculationRequestLoading = true

        const payload = { order_items: $scope.modifyOrderItems }

        if (cartService.dateOfBirth) {
          payload.user_birthday = dayjs(cartService.dateOfBirth, DATE_OF_BIRTH_FORMAT)
            .startOf('day')
            .toISOString()
        }

        Restangular.one('orders', $scope.order.id)
          .post('calculate_totals', payload)
          .then(response => {
            $scope.setEstimatedOrderTotals(response.estimated_totals)

            lastValidCalculateResponse = response

            $scope.modifyOfferProgress = response.offer_progress

            response.order_items.forEach(item => {
              if (!$scope.getItemLineNumber(item)) return
              $scope.updateModifyItem($scope.getItemLineNumber(item), { sub_total: item.sub_total })
            })

            if ($scope.isCalculationRequestBuffered) {
              $scope.handleCalculateOrderTotals()
              $scope.isCalculationRequestBuffered = false
            }
          })
          .catch(error => {
            if (error?.data?.message) errorModalService.open(error.data.message, lang.values.ok)

            $scope.modifyOrderItems = cloneDeep(lastValidCalculateResponse.order_items)
            $scope.updateCategorizedItems()
          })
          .finally(() => {
            $scope.isCalculationRequestLoading = false
          })
      }

      $scope.debouncedCalculateOrderTotals = debounce($scope.calculateOrderTotals, 500)

      /**
       * Calculate totals and update order item sub totals,
       * with loading and debouncing.
       */
      $scope.handleCalculateOrderTotals = () => {
        $scope.isCalculationRequestDebounced = true
        $scope.debouncedCalculateOrderTotals()
      }

      // When Offer service updates the coupon clipping state,
      // then recalculate totals to get the latest value after the coupon discount is applied
      $rootScope.$on('couponOfferClippedStateUpdated', () => {
        $scope.handleCalculateOrderTotals()
      })

      /**
       * Uses calculation request debounced status as well as total calculation requests to determine loading state.
       *
       * @returns {boolean} Loading state
       */
      $scope.isCalculationLoading = () =>
        $scope.isCalculationRequestDebounced || $scope.isCalculationRequestLoading

      const postOrder = (orderId, orderItems) => {
        const payload = { order_items: orderItems }

        if (cartService.dateOfBirth) {
          payload.user_birthday = dayjs(cartService.dateOfBirth, DATE_OF_BIRTH_FORMAT)
            .startOf('day')
            .toISOString()
        }

        return Restangular.one('orders', orderId).post('modify', payload)
      }

      /**
       * Save changes and redirect back to the order.
       */
      $scope.saveOrderModifications = () => {
        $rootScope.isAppLoading = true
        const isModified = !isEqual($scope.modifyOrderItems, originalOrderItems)

        Promise.resolve()
          .then(() => isModified && postOrder($scope.order.id, $scope.modifyOrderItems))
          .then(() =>
            $state.go(
              'main.account.orderHistory.order',
              {
                id: $scope.order.id,
                message: isModified ? lang.values.orderModifiedSuccess : null,
              },
              { reload: true }
            )
          )
          .catch(error => {
            errorModalService.open(error.data?.message, lang.values.ok)
          })
          .finally(() => {
            $rootScope.isAppLoading = false
          })
      }

      /**
       * Redirect back to the order without saving changes.
       */
      $scope.discardOrderModifications = () => {
        $state.go('main.account.orderHistory.order', {
          id: $scope.order.id,
        })
      }

      /**
       * Go to view to add items
       */
      $scope.addItemsToOrder = () => {
        const { order, modifyOrderItems } = $scope
        $rootScope.orderEditItemStash = { order, modifyOrderItems }
        $state.go('main.account.orderHistory.order.edit.append', { id: order.id })
      }

      /**
       * Checks whether an offer exists for a given item.
       *
       * @param {object} item - Cart item to check for offer
       */
      $scope.itemHasOffer = item =>
        cartService.getCartItemIdsFromOffers($scope.modifyOfferProgress).includes(item.id)

      /*
       * INITIALIZE STATE
       */
      $scope.updateCategorizedItems().then($scope.handleCalculateOrderTotals)
    }
  )
  .run($templateCache => {
    $templateCache.put('orderEditItem.html', require('./views/orderEditItem.html'))
  })

/**
 * Converts a new item into a semi-edit item for processing by the calculate_totals API
 * @param {object} newItem
 * @param {number} totalItemCount
 * @returns {object}
 */
function convertNewItemToEditItem(newItem, totalItemCount) {
  return {
    ...newItem,
    ext_data: {}, // TODO HOW DO WE GET DATA HERE
    line_number: `${totalItemCount + 1}`,
    status: 'original',
    allow_substitutions: true,
    comment: null,
    customer_comment: null,
    uom: (newItem.store_product.display_uom || UNIT_OF_MEASURE.EACH).toUpperCase(),
    sub_total: newItem.store_product.base_price * newItem.quantity,
    actual_quantity: newItem.quantity,
  }
}
