import _ from 'lodash'
import fp from 'lodash/fp'
// This is the root component for faceted search. It requires the array of filterable
// tags for a given client and the results of the /facets GET request.
import angular from 'angular'
import { getStickyHeaderHeight } from '@app/utils/getStickyHeaderHeight'
import template from './facetedFiltersBar.html'

export default angular.module('FacetedFiltersBarModule', []).component('facetedFiltersBar', {
  template,
  bindings: {
    tags: '<',
    showSort: '<',
    subset: '<',
  },
  controller(
    $scope,
    $state,
    $stateParams,
    auth,
    config,
    eComLightService,
    facetedSearchService,
    lang,
    utilService
  ) {
    $scope.lang = config.lang
    const vm = this

    vm.inEComLightMode = eComLightService.inEComLightMode
    vm.lang = lang

    // Component state variables
    vm.selectedTags = {}
    vm.selectedBrands = []
    vm.showSmallDropdown = false
    vm.showFullDropdown = false
    vm.extraFiltersOpen = false
    vm.subset = vm.subset || 'search' // This is the context where this page is operated on. Could be 'shop' or 'search'. Default 'search'.

    // Initialize sorter
    vm.state = $state
    vm.sortOptions = config.sortOptions
    vm.sortOptions =
      fp.flow(
        fp.filter(option => _.includes(option.showIn, vm.subset)),
        fp.sortBy(option => option.order)
      )(config.sortOptions) || []

    // Set sort order, using a default value if one isn't provided

    let sortValue = $stateParams.sort

    if (!sortValue) {
      const authMode = auth.user.status === 'trial' ? 'trial' : 'loggedIn'
      sortValue = config.defaultSort[authMode][vm.subset]
    }

    const filteredValue = _.filter(vm.sortOptions, { value: sortValue })

    vm.sort = filteredValue[0]

    vm.sortChange = () => {
      $state.go('.', { sort: vm.sort.value }).then(() => {
        utilService.focusNodeWhenInDOM(`.faceted-sort-dropdown select`)
      })
    }

    if ($stateParams.tags) {
      if (_.isArray($stateParams.tags)) {
        _.each($stateParams.tags, tag => {
          vm.selectedTags[tag] = true
          facetedSearchService.setTagCrumbs(true, tag, 'tags')
        })
      } else {
        vm.selectedTags[$stateParams.tags] = true
        facetedSearchService.setTagCrumbs(true, $stateParams.tags, 'tags')
      }
    }

    // Wait for `facetedSearchService.getAllFacets` to successfully return data in `searchController`
    $scope.$on('facetsDataReceived', () => {
      // Facets based on categories
      // 'Categories' is always returned from BE and is safe to hardcode
      vm.categoriesFacetObject = facetedSearchService.categorizedFacetsObject.categories
    })

    $scope.stickyOffset = getStickyHeaderHeight($scope.inEComLightMode)
  },
})
