import angular from 'angular'

export default angular.module('UnataNavToggleButtonModule', []).component('unataNavToggleButton', {
  controller($scope, mobileNavService, unataCommService) {
    $scope.slideOutMenu = false

    this.toggleNav = () => {
      mobileNavService.toggleSlideOutMenu()

      const payload = { showMobileSideNav: true }
      unataCommService.send({
        eventType: unataCommService.EventType.TOGGLEMOBILESIDENAV,
        payload,
      })
    }
  },
  template: `
      <div role="button" class="icon-menu" ng-click="$ctrl.toggleNav()"></div>
    `,
})
