import _ from 'lodash'
import angular from 'angular'
import template from './pageHeader.html'

export default angular
  .module('PageHeaderComponentModule', [])
  .directive('pageHeader', function () {
    return {
      scope: {
        label: '=',
        loading: '=',
        background: '=',
        breadcrumbs: '=',
        button: '=',
        link: '=',
        previousPage: '=',
        hideMobileAnimation: '<',
        flyerData: '<?',
        previewToken: '@?',
        noBackgroundBorder: '=',
        searchReformulationText: '=',
        selectedFilters: '<',
        showSearchTabs: '=?',
        initialSearchTerm: '=',
        isSpellCorrected: '=',
        searchSpellCorrectionDisabledSref: '=',
      },
      transclude: true,
      replace: 'element',
      template,
      controller(
        $filter,
        $scope,
        $state,
        config,
        eComLightService,
        facetedSearchService,
        lang,
        mobileService,
        utilService,
        searchService
      ) {
        $scope.lang = lang
        $scope.mobileService = mobileService
        $scope.state = $state
        $scope.utilService = utilService
        $scope.tabs = searchService.getTypes()
        $scope.facetedSearchService = facetedSearchService
        $scope.config = config

        $scope.enableBackground = mobileService.isMobile
          ? config.enablePageHeaderBackground?.mobile
          : config.enablePageHeaderBackground?.desktop
        $scope.tabHrefParamCategoryId =
          eComLightService.inEComLightMode && eComLightService.eComLightCategory
            ? eComLightService.eComLightCategory
            : null

        // Explicitly stop breadcrumbs from inheiriting a page param, we always
        // want breadcrumbs to direct the user to the first page for paginated items.
        if ($scope.breadcrumbs) {
          _.forEach($scope.breadcrumbs, breadcrumb => {
            if (breadcrumb.params) {
              breadcrumb.params.page = null
            } else {
              breadcrumb.params = { page: null }
            }
          })
        }

        $scope.hasFacetedSearchFilters = config.enableFacetedSearchFilters || config.enableICSearch
        $scope.isSearchState = $filter('includedByState')('main.search')

        if ($scope.showSearchTabs === undefined) {
          $scope.showSearchTabs = searchService.hasMultipleTypes() && $scope.isSearchState
        }

        $scope.activeTab = searchService.getTypeBy($state.current.name, 'state')

        $scope.hasMultipleSearchTabs = searchService.hasMultipleTypes()

        if (mobileService.isMobile) {
          const getFacetDisplayName = name => {
            return $scope.facetedSearchService.getFacetDisplayName(name)
          }

          $scope.getFacetDisplayNames = names => {
            const displayNames = []
            if (names) {
              const nameArray = names.split(', ')
              nameArray.forEach(name => {
                displayNames.push(getFacetDisplayName(name))
              })
              return displayNames.toString().replace(/,/g, ', ')
            }
            return null
          }
          // Figure out back button behaviour for mobile

          const currentBreadcrumb =
            $scope.breadcrumbs && $scope.breadcrumbs[$scope.breadcrumbs.length - 1]
          const currentBreadcrumbCategoryId =
            (currentBreadcrumb &&
              currentBreadcrumb.params &&
              currentBreadcrumb.params.category_id) ||
            null
          $scope.shouldShowMobileBackButton = () =>
            !(
              eComLightService.inEComLightMode &&
              currentBreadcrumbCategoryId &&
              currentBreadcrumbCategoryId === eComLightService.eComLightCategory
            )

          $scope.mobileGoBack = () => {
            if ($scope.previousPage) {
              utilService.goBack()
            } else {
              // The breadcrumb that comes right before the current page
              const breadcrumbParent =
                $scope.breadcrumbs && $scope.breadcrumbs.length > 1
                  ? $scope.breadcrumbs[$scope.breadcrumbs.length - 2]
                  : null

              // Direct the user to the breadcrumbParent state/page unless it's the Ways to Shop page (which we
              //  don't want to show in mobile), or unless the breadcrumb state does not contain a route
              // TODO: Perhaps find a way to erase the 'main.shop' route from the breadcrumbs altogether if the user is in mobile
              // TODO: Eventually we should probably avoid using breadcrumbs that do not contain their corresponding state
              if (
                breadcrumbParent &&
                breadcrumbParent.state &&
                breadcrumbParent.state !== 'main.shop'
              ) {
                $state.go(breadcrumbParent.state, breadcrumbParent.params)
              } else {
                $state.go('main.home')
              }
            }
          }
        }
        $scope.showNewTag = tab =>
          config.searchTabTag.enabled &&
          config.searchTabTag.tab.toLowerCase() === tab.label.toLowerCase()
      },
    }
  })
  .run($templateCache => {
    $templateCache.put('pageHeader.desktop.html', require('./pageHeader.desktop.html'))
    $templateCache.put('pageHeader.mobile.html', require('./pageHeader.mobile.html'))
    $templateCache.put(
      'pageHeader.search.desktop.html',
      require('./pageHeader.search.desktop.html')
    )
    $templateCache.put('pageHeader.search.mobile.html', require('./pageHeader.search.mobile.html'))
  })
