import angular from 'angular'

import { LOYALTY_ONBOARDING_STEPS } from '@app/angular/constants/loyaltyOnboarding'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import template from './views/loyaltyOnboardingModal.html'
import LoyaltyOnboardingModalControllerModule from './controllers/loyaltyOnboardingModalController'

export default angular
  .module('LoyaltyOnboardingServiceModule', [LoyaltyOnboardingModalControllerModule.name])
  // Service to power the loyalty onboarding flow.
  .service(
    'loyaltyOnboardingService',
    function loyaltyOnboardingService(
      $q,
      $rootScope,
      $uibModal,
      auth,
      config,
      importIcAccountService,
      lang,
      uibModalAriaLabelService,
      userAnalytics
    ) {
      const service = this

      service.hasSeenLoyalty = false

      service.initOnboarding = function (
        step,
        singleView,
        errorMessage,
        socialSignOnData,
        context
      ) {
        service.hasSeenLoyalty = true
        // When registrationService.open() is called on the /account/profile page (logged out),
        // the loading state is never reset. This leaves an empty loading div in the code
        // Not an issue visually, but this is causing cypress tests to fail because the element exists.
        $rootScope.isAppLoading = false

        return (
          $uibModal
            .open({
              template,
              controller: 'LoyaltyOnboardingModalController',
              keyboard: true,
              backdrop: 'static',
              resolve: {
                ariaLabel: () => {
                  uibModalAriaLabelService.setLabel(lang.values.welcome)
                },
                loyaltyOnboardingStep: () => step,
                hideRegistration: () => singleView === LOYALTY_ONBOARDING_STEPS.LOGIN,
                errorMessage: () => errorMessage,
                socialSignOnData: () => socialSignOnData,
                context: () => context,
              },
            })
            /**
             * This modal can be dismissed at the 'registration' step (abandonment)
             * or at the 'thankyou' step (successfully registered). Dismissal does not
             * mean we do not show account import modal
             */
            .result.catch(step => {
              // Add analytics for dismissing loyalty onboarding modal
              if (context === LOGIN_CONTEXTS.LOYALTY_ONBOARDING) {
                userAnalytics.logUserLoyaltyOnboardingExit({ source: step })
              }

              return { step, dismissed: true }
            })
            .then(({ step, dismissed }) => {
              // Need to check if the user has successfully registered
              if (step === LOYALTY_ONBOARDING_STEPS.THANKYOU) {
                return importIcAccountService.openModal(LOGIN_CONTEXTS.ON_REGISTRATION)
              }

              // Pass along reject promise so it can be handled by caller for dismiss events
              if (dismissed) {
                return Promise.reject(step)
              }
            })
        )
      }
    }
  )
