import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'
import tagHelper from '@app/angular/reactComponents/checkbox/tagHelper'
import template from './filterModal.html'

export default angular
  .module('FilterModalComponentModule', [])
  .controller(
    'filterModal',
    function filterModal(
      $scope,
      $state,
      $stateParams,
      $uibModalInstance,
      config,
      filterModalOptions,
      lang,
      tagsService
    ) {
      this.dismissModal = $uibModalInstance.dismiss
      this.config = config
      this.lang = lang
      this.sortOptions =
        filterModalOptions.subset && !filterModalOptions.hideSort
          ? fp.flow(
              fp.filter(option => _.includes(option.showIn, filterModalOptions.subset)),
              fp.sortBy(option => option.order)
            )(config.sortOptions)
          : []

      this.tagOptions = filterModalOptions.tagOptions.map(tag => {
        const fullTag = tagsService.byVal(tag.id)
        // tagHelper applies the correct styles to the tag based on the config
        const tagStyles = tagHelper(fullTag)
        return (tag = { ...fullTag, ...tagStyles })
      })

      this.brandOptions = _.concat(
        [{ name: 'All Brands', value: null }],
        filterModalOptions.brandOptions || []
      )

      this.model = {
        content: filterModalOptions.content,
        sort: filterModalOptions.sort || filterModalOptions.defaultSort || null,
        tags: _.zipObject(
          filterModalOptions.tags,
          _.times(_.get(filterModalOptions.tags, 'length', 0), _.constant(true))
        ),
        brand: filterModalOptions.brand || filterModalOptions.defaultBrand || null,
      }

      this.reset = () => {
        this.model.content = filterModalOptions.content
        this.model.sort = filterModalOptions.defaultSort || null
        this.model.tags = {}
        this.model.brand = filterModalOptions.defaultBrand || null
      }

      const searchTerm = $stateParams.search_term

      this.applyFilters = function () {
        $uibModalInstance.close({
          content: this.model.content,
          sort: this.model.sort,
          tags: _.keys(_.pickBy(this.model.tags)),
          brand_name: this.model.brand,
          search_term: searchTerm,
        })
      }

      this.keyboardToggleTagOption = tagOptionId => {
        if (this.model.tags.hasOwnProperty(tagOptionId)) {
          this.model.tags[tagOptionId] = !this.model.tags[tagOptionId]
        } else {
          this.model.tags[tagOptionId] = true
        }
      }

      this.keyboardSelectBrandOption = brandOptionValue => {
        this.model.brand = brandOptionValue
      }

      // Show registration modal if they click on a filter that requires registration

      $scope.$watch(
        'this.model.tags',
        (newTags, oldTags) => {
          if (newTags && newTags !== oldTags) {
            tagsService.checkRegistrationRequired({
              activeTags: this.model.tags,
              allTags: this.tagOptions,
              success: loginWasRequired => {
                if (loginWasRequired) {
                  const activeTags = Object.keys(newTags).filter(key => Boolean(newTags[key]))
                  $state.go('.', { tags: activeTags, page: null })
                }
              },
            })
          }
        },
        true
      )
    }
  )

  .service('filterModalService', function filterModalService($uibModal, uibModalAriaLabelService) {
    /**
     * Opens a filter modal dialog.
     * @param {FilterOptions} options Options to pass to modal
     * @return {Promise<FilterResults>} The new filter options to be used
     */
    this.open = function (options) {
      return $uibModal.open({
        template: template,
        controller: 'filterModal',
        controllerAs: '$ctrl',
        resolve: {
          filterModalOptions: function () {
            return options
          },
          ariaLabel: () => {
            uibModalAriaLabelService.setLabel('Filters')
          },
        },
      }).result
    }

    return this
  })
