import angular from 'angular'
import { user } from '@instacart/enterprise-services'
import { GET_PAY_SECURE_URL } from '@app/angular/constants/paymentGateway'
import { USER_ACTIONS } from '../../constants/events'

export default angular
  .module('ebtCardModule', [])
  .service(
    'ebtCardService',
    function ebtCardService($rootScope, config, oneCartService, userAnalytics, utilService) {
      this.userHasEbtCard = false
      this.lastFourEbtDigits = null
      this.instrumentId = null

      const clearEbtCard = () => {
        this.userHasEbtCard = false
        this.lastFourEbtDigits = null
        this.instrumentId = null
      }

      const serializeEbtCardState = () => {
        return {
          userHasEbtCard: this.userHasEbtCard,
          lastFourEbtDigits: this.lastFourEbtDigits,
          instrumentId: this.instrumentId,
        }
      }

      const setEbtCard = (userHasEbtCard, lastFourEbtDigits, instrumentId) => {
        this.userHasEbtCard = userHasEbtCard
        this.lastFourEbtDigits = lastFourEbtDigits
        this.instrumentId = instrumentId
      }

      // Fetch the user's EBT Card on changes to the auth object.
      $rootScope.$on('auth', (event, action) => {
        if (action !== USER_ACTIONS.USER_LOGGING_OUT && oneCartService.enabled()) {
          this.getEbtCard()
        }
      })

      /**
       * Fetches the current user EBT card
       * @return {Promise}
       * {
       *   card_last_four: string
       *   instrument_id: integer
       *   label: string
       * }
       */
      this.getEbtCard = () => {
        return user.api.user.getUserEbtCard().then(card => {
          if (card.card_last_four) {
            setEbtCard(true, card.card_last_four, card.instrument_id)
            // Broadcast the event for listeners to update EBT card details.
            $rootScope.$broadcast('ebtCard', serializeEbtCardState())
          }
        })
      }

      /**
       * Removes the current user EBT card
       * @returns {Promise}
       * {}
       */
      this.removeEbtCard = () => {
        return user.api.user.deactivateEbtCard({ ebt_card_id: this.instrumentId }).then(() => {
          clearEbtCard()
          $rootScope.$broadcast('ebtCard', serializeEbtCardState())
        })
      }

      /**
       * Creates a new EBT card
       * @param {string} cardNumber - the EBT card number
       * @param {function} successCallback - optional success callback
       *
       * @returns {Promise}
       * {
       *   card_last_four: string
       *   instrument_id: integer
       *   label: string
       * }
       */
      this.addCard = cardNumber => {
        return user.api.user
          .getEbtSessionToken()
          .then(tokenResponse => {
            return tokenResponse.ebt_session_id
          })
          .then(sessionId => {
            return user.api.user
              .clientTokenizeCard(
                {
                  SessionId: sessionId,
                  CardNumber: cardNumber,
                  Expiration: '1223', // Any 4 digits
                },
                `${GET_PAY_SECURE_URL(utilService.isProd())}/ClientTokenizeCard`
              )
              .then(tokenizedResponse => {
                // eslint-disable-next-line eqeqeq
                if (tokenizedResponse.ErrorMessage == 'Success') {
                  return sessionId
                }
                userAnalytics.logUserEbtClientTokenizeFailed({
                  error_code: tokenizedResponse.ErrorCode,
                })
              })
          })
          .then(sessionId => {
            return user.api.user.createCardToken({
              ebt_session_id: sessionId,
            })
          })
          .then(createCardResponse => {
            userAnalytics.logUserAddEbtCard()
            setEbtCard(true, createCardResponse.card_last_four, createCardResponse.instrument_id)
          })
      }
    }
  )
