import angular from 'angular'
import template from './icExpressPlans.html'

export default angular.module('IcExpressPlansComponentModule', []).component('icExpressPlans', {
  template,
  bindings: {
    plans: '<',
    useIcTheme: '<',
    onPlanSelect: '&',
    selectedPlan: '<',
  },
  controller(config, lang) {
    this.$onInit = () => {
      this.expressConfig = config.icExpress
      this.lang = lang
    }

    this.getButtonLabel = plan => {
      const { keep, select } = this.lang.values
      const action = plan.id === this.selectedPlan.id ? keep : select

      return this.lang.templateAsString(this.lang.values.icExpress.planLabel, {
        action,
        plan: plan.name,
      })
    }
  },
  controllerAs: '$ctrl',
})
