import angular from 'angular'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import registrationTemplate from './views/registration.html'

export default angular
  .module('RegistrationServiceModule', [])
  .service(
    'registrationService',
    function registrationService(
      $injector,
      $rootScope,
      $state,
      $uibModal,
      $window,
      auth,
      config,
      lang,
      legacyModalService,
      localStorageService,
      loyaltyOnboardingService,
      uibModalAriaLabelService,
      unataCommService,
      userAnalytics
    ) {
      'ngInject'

      const service = this

      // Checks if an optional auth service is enabled. We cannot inject this on controller instantiation
      // since the existence of the service is dynamic, based on whether or not it has been included in
      // optionalComponents
      service.hasOptionalAuthService = function () {
        const authService = auth.getAuthService()
        return !!authService
      }

      service.open = function (
        view,
        disableClose,
        singleView,
        errorMessage,
        socialSignOnData,
        subscriptionCode,
        context
      ) {
        const authService = auth.getAuthService()

        legacyModalService.closeOpenModals()
        service.view = view

        // If we are in loyalty onboarding mode, open alternate login/forgot/register modal view

        if (authService) {
          localStorageService.deprecatedSet('loginContext', context)
          return authService.init(view)
        }
        if (config.enableLoyaltyOnboarding) {
          return loyaltyOnboardingService.initOnboarding(
            view,
            singleView,
            errorMessage,
            socialSignOnData,
            context
          )
        }
        const modal = $uibModal.open({
          template: registrationTemplate,
          controller: 'RegistrationController',
          resolve: {
            ariaLabel: () => {
              uibModalAriaLabelService.setLabel(lang.values.loginOrRegister)
            },
            disableClose: () => disableClose,
            singleView: () => singleView,
            error: () => errorMessage,
            view: () => view,
            socialSignOnData: () => socialSignOnData,
            subscriptionCode: () => subscriptionCode,
          },
          keyboard: !disableClose,
          backdrop: disableClose ? 'static' : true,
        })

        // Handle callback functions on modal events.
        // TODO: We should reconsider the use of tabs / overloading registration modals, they
        // have behaviour split between this service and individual controllers right now.
        // If we see further bugs with registration exits, this is a good place to start looking.
        modal.result.then(
          () => {
            // This first callback will fire on close events, there are none when abandoning the flow.
          },
          () => {
            // This second callback will fire on dismiss events.
            if (service.view === 'register') {
              userAnalytics.logUserRegistrationExit()
            }
          }
        )

        return modal.result
      }

      // If user session times out, force user to login
      service.promptLogin = function (
        toState,
        toParams,
        fromState,
        fromParams,
        errorMessage,
        loginContext = LOGIN_CONTEXTS.PROMPT_LOGIN
      ) {
        return service.open('login', true, 'login', errorMessage, null, null, loginContext).then(
          () => {
            $rootScope.isAppLoading = false
            if (toState) {
              return $state.go(toState, toParams)
            }
            return $state.go('main.home', null, {
              reload: true,
            })
          },
          () => {
            $rootScope.isAppLoading = false
            if (!fromState) {
              return $state.go('main.home', null, {
                reload: true,
              })
            }
          }
        )
      }

      // Listen for requests to open the login/register modal
      // Note to self - this doesn't look like it's being used
      unataCommService.listen(unataCommService.EventType.OPENLOGINREGISTERMODAL, event => {
        const targetScreen = event.data.payload ? event.data.payload.targetScreen : 'login'
        if (['login', 'register'].includes(targetScreen)) {
          service.open(targetScreen)
        }
      })

      // Listen for events outside of Angular app
      // We need a better way to do this
      // Going to avoid rewriting/introducing more code for now
      eventEmitter.on(EVENT_TYPES.MODAL_OPEN_REGISTRATION_LOGIN, ({ view, context }) =>
        service.open(view, null, null, null, null, null, context)
      )
    }
  )
