import _ from 'lodash'
import angular from 'angular'
import template from './deliveryForm.html'

const DELIVERY_FORM_ID = 'delivery'

class DeliveryForm {
  constructor(
    $filter,
    $q,
    addressList,
    auth,
    checkoutAnalytics,
    checkoutService,
    compareCartService,
    config,
    lang,
    storesService,
    utilService
  ) {
    Object.assign(this, {
      $filter,
      $q,
      addressList,
      auth,
      checkoutAnalytics,
      checkoutService,
      compareCartService,
      config,
      isEditingAddress: false,
      errors: [],
      lang,
      storesService,
      utilService,
      isLoading: false,
    })
  }

  /**
   * Gets a human readable string from an address object
   *
   * @param { city, province, postal_code } address
   */
  addressAsString({ address1, city, province, postal_code }) {
    if (!address1 || !city || !province || !postal_code) {
      return ''
    }

    return `${address1}<br />${city}, ${province} ${postal_code}`
  }

  /**
   * Gets a human readable string from an address object for contact information
   *
   * @param { first_name, last_name, phone_number } address
   */
  contactInformationAsString({ first_name, last_name, phone_number }) {
    if (!first_name || !last_name) {
      return ''
    }

    let contactInfo = `${first_name} ${last_name}`

    if (phone_number) {
      contactInfo += `, ${this.$filter('telephone')(phone_number)}`
    }

    return contactInfo
  }

  /**
   * Returns whether an address is valid or not
   * Only checks if the address object is empty
   *
   * @param address object
   * @returns { Boolean }
   */
  isValid(address) {
    return !_.isEmpty(address)
  }

  /**
   * Returns whether an address phone number is valid or not
   * Only checks if the address.phone_number is empty
   *
   * @param address object
   * @returns { Boolean }
   */
  isValidPhoneNumber(address) {
    return !_.isEmpty(address) && !_.isEmpty(address.phone_number)
  }

  /**
   * Returns whether we can deliver to a postal code or not
   *
   * @param { object } address - Address object that has *postal_code* attribute
   * @returns { Promise }
   */
  isValidPostalCode({ postal_code }) {
    const userStore = this.auth.user.store
    const payload = {
      delivery_postal_code: postal_code,
      show_delivery: true,
    }

    if (userStore && userStore.store_banner && userStore.store_banner.key) {
      payload.retailer_slug = userStore.store_banner.key
    }

    return this.storesService
      .fetch(payload)
      .then(stores => {
        // eslint-disable-next-line no-throw-literal
        if (!stores || stores.length === 0) throw null

        return stores
      })
      .catch(() => this.handleInvalidPostalCodeError())
  }

  /**
   * Handles postal code validation errors
   *
   * @returns {undefined}
   */
  handleInvalidPostalCodeError() {
    this.errors.push(this.lang.values.noDeliveryToPostalCodeError)
    this.onError()
  }

  /**
   * Changes the section to edit mode by calling the
   * edit prop function
   *
   * @returns { undefined }
   */
  enterEditMode() {
    this.edit({ step: DELIVERY_FORM_ID })
    this.checkoutAnalytics.viewFulfillmentOptions()
  }

  /**
   * Sets the address to whatever is selected by the
   * address list component.
   *
   * Sets the *isEditingAddress* prop to `false` to show
   * the save button
   *
   * @param { Object } address
   * @param { boolean } saveSelectedAddress Whether to save the set address and exit the form.
   * @returns { undefined }
   */
  setDeliveryAddress(address, saveSelectedAddress) {
    this.address = address
    this.isEditingAddress = false

    if (saveSelectedAddress) {
      this.saveAddress(address)
    }
  }

  /**
   * This method is passed to the addressList component and it gets called
   * whenever the user opens the form to edit an existing address
   *
   * Sets the *isEditingAddress* to `true` to hide the save button.
   *
   * @returns { undefined }
   */
  editingDeliveryAddress(address) {
    if (!address) {
      this.isEditingAddress = false
    } else {
      this.isEditingAddress = true
    }
  }

  /**
   * Save the currently selected address to the user context
   * then exits out of the edit state
   *
   * @param { Object } address
   * @returns { undefined }
   */
  saveAddress(address) {
    /**
     * Returns whether there are cart changes for the new address
     * and if the user accepts these changes
     *
     * @param Object store
     * @param Object address
     * @returns { Promise }
     */
    const storeCartCompare = (store, address) => {
      return this.compareCartService.compareCart(true, 'store', {
        store_id: store.id,
        delivery_postal_code: address.postal_code,
      })
    }

    this.errors = []

    this.isLoading = true

    const initialStore = this.auth.user.store
    let newStore

    return this.isValidPostalCode(address, this.auth.user)
      .then(stores => {
        newStore = stores[0]
        return storeCartCompare(newStore, address)
      })
      .then(({ shouldRecalculateFees }) => {
        this.checkoutService.handleCartCompareChange(shouldRecalculateFees)
      })
      .then(async () => {
        const newAddress = this.auth.getContextFromAddress(address)
        const newContext = { ...this.auth.getContext(), ...newAddress, storeNumber: newStore.id }

        await this.auth.setContext(newContext)
        await this.auth.update({
          delivery_address: address,
          context: newContext,
          store: newStore,
          store_id: newStore.id,
        })

        // This is behind a config as this needs to be backported to 2.29 and it would be really risky to turn it on for every client
        // TODO: See if this would break anything on other clients
        if (this.config?.saveAddressUserProfile) {
          await this.auth.updateProfile({
            delivery_address: address,
          })
        }

        return address
      })
      .then(add => {
        const hasStoreChanged = newStore.id !== initialStore.id
        return this.onAddressApplied({ address: add, hasStoreChanged })
      })
      .then(() => this.checkoutAnalytics.editFulfillmentOptions({ success: true }))
      .catch(() => {
        this.onError()
        this.checkoutAnalytics.editFulfillmentOptions({ success: false })
      })
      .finally(() => {
        this.isLoading = false
      })
  }
}

export default angular.module('DeliveryFormModule', []).component('deliveryForm', {
  bindings: {
    address: '<',
    edit: '&',
    editMode: '<',
    onAddressApplied: '&',
    onError: '&',
    onCancel: '&',
    hideChevron: '<',
  },
  template,
  controller: DeliveryForm,
})
