import _ from 'lodash'
import angular from 'angular'
import { user } from '@instacart/enterprise-services'
import { ADDRESS_FIELDS } from '@app/angular/constants/address'

import template from './views/addressValidationModal.html'

export default angular
  .module('AddressValidationServiceModule', [])
  .service(
    'addressValidationService',
    function addressValidationService($uibModal, ngPromise, utilService) {
      const service = this
      const ADDRESS_KEYS = [
        ADDRESS_FIELDS.PROVINCE,
        ADDRESS_FIELDS.POSTAL_CODE,
        ADDRESS_FIELDS.CITY,
        ADDRESS_FIELDS.ADDRESS_LINE_ONE,
        ADDRESS_FIELDS.ADDRESS_LINE_TWO,
      ]

      service.openModal = addressData => {
        return $uibModal.open({
          template,
          controller: 'AddressValidationModalController',
          keyboard: true,
          backdrop: true,
          controllerAs: '$ctrl',
          resolve: {
            originalAddress: () => addressData.original,
            recommendedAddresses: () => addressData.recommended,
            isValid: () => addressData.isValid,
          },
        }).result
      }

      //  validateAddress - Accepts a JSON object that contains the customers address and returns
      //  a flag to identify if it is valid, and an array of suggested addresses
      //  example input addressObject
      //  {
      //  "address1": "101 S, W Compton Blvd",
      //  "address2": "123",
      //  "city": "comptonnne",
      //  "province": "CAliforn-i-eh",
      //  "postal_code": "90220"
      //  }
      //
      //  returns:
      //  { "is_valid": true,
      //    "suggestions": [{"address1": "# 123",
      //                    "address2": "101 W COMPTON BLVD",
      //                    "city": "COMPTON",
      //                    "postal_code": "90220",
      //                    "province": "CA"}]
      // }
      const validateAddress = ngPromise.wrapper(user.api.userAddresses.validate)
      service.validateAddress = addressObject => validateAddress(addressObject)

      service.areAddressesSame = (firstAddress, secondAddress) => {
        return _.every(ADDRESS_KEYS, key => firstAddress[key] === secondAddress[key])
      }

      service.createRecommendationsList = (suggestions, currentAddress) => {
        return _.map(suggestions, suggestedAddress => {
          const addressDiffObject = utilService.stringDiff(currentAddress, suggestedAddress)
          const noDiffs = _.every(addressDiffObject, addressValue => {
            return !addressValue.changed
          })

          if (!noDiffs) {
            return addressDiffObject
          }
        }).filter(Boolean)
      }

      /**
       * Validates if a given *address* is complete when
       * compared agains a list of *optionalFields*
       * @param {object} - address
       * @param {string[]} - optionalFields
       *
       * @returns {boolean}
       */
      service.isAddressComplete = (address = {}, optionalFields = []) => {
        const REQUIRED_FIELDS = [
          ADDRESS_FIELDS.ADDRESS_LINE_ONE,
          ADDRESS_FIELDS.CITY,
          ADDRESS_FIELDS.FIRST_NAME,
          ADDRESS_FIELDS.LAST_NAME,
          ADDRESS_FIELDS.PHONE_NUMBER,
          ADDRESS_FIELDS.POSTAL_CODE,
          ADDRESS_FIELDS.PROVINCE,
        ]

        const filteredRequiredFields = REQUIRED_FIELDS.filter(
          field => !optionalFields.includes(field)
        )

        return filteredRequiredFields.filter(addressKey => !address[addressKey]).length === 0
      }
    }
  )
