import angular from 'angular'
import * as storage from '@app/utils/storage'
import { legacyStorage } from '@instacart/enterprise-services'

export default angular
  .module('LocalStorageModule', [])
  .service('localStorageService', function localStorage() {
    const storageService = storage.getInstance()
    const legacyStorageService = legacyStorage()

    // Preferred - Use storage service and are async APIs that return promises
    this.getItem = storageService.getItem
    this.setItem = storageService.setItem
    this.removeItem = storageService.removeItem
    this.clear = storageService.clear
    this.keys = storageService.keys

    // Avoid - Use legacyStorage service and are synchronous API
    this.deprecatedGet = legacyStorageService.getItem
    this.deprecatedSet = legacyStorageService.setItem
    this.deprecatedRemove = legacyStorageService.removeItem
    this.deprecatedClear = legacyStorageService.clear
    this.deprecatedKeys = legacyStorageService.keys
  })
