import angular from 'angular'
import { WEGPAY_SUCCESS } from '@app/angular/constants/wegpay'
import template from './wegpayIntegration.html'

export default angular
  .module('WegpayPaymentServiceModule', [])
  .config(paymentGatewayProvider => {
    paymentGatewayProvider.registerGateway({
      name: 'Wegpay',
      key: 'auth_capture',
      template,
      serviceName: 'WegpayPayment',
      newCheckoutCssClass: 'provider-generic_styling',
      hideSaveButton: true,
    })
  })
  .service('WegpayPayment', function WegpayPayment($q, defaultPayments) {
    const service = this

    service.isReady = false
    service.status = {}
    service.checkout = defaultPayments.checkout
    service.paymentInstruments = []
  })

/**
 * TODO ENSURE THAT NOTHING ANGULAR APPEARS BELOW THIS LINE
 *      This code is to make upgrading in the future easier!
 */

/**
 * This imports the wegpay dependencies (jquery and the javascript file)
 * You can call this multiple times but it will only load the resources if they are missing
 *
 * Upon subsequent calls it will return a cached promise, as per the "promiseable" util
 *
 * @param {object} utilService       - The util service containing "loadScript", "semVerDifference" methods
 * @param {string} iFrameJSsrc       - The string of the iframe javascript src
 * @param {string} minJQueryVersion  - The version string for the minimum supported jQuery version for Wegpay
 * @param {string} jQuerySrc         - The jQuery source file to load if jQuery is not present already
 *
 * @returns {Promise}            - Resolves if successful
 *
 */
export const importWegpayDependencies = async (
  { loadScript, semVerDifference },
  iFrameJSsrc,
  minJQueryVersion,
  jQuerySrc
) => {
  const hasJquery = typeof window.jQuery !== 'undefined'
  const hasWegpay = typeof window.EprotectIframeClient !== 'undefined'

  if (hasJquery && semVerDifference(window.jQuery.fn.jquery, minJQueryVersion) < 0) {
    throw new Error(
      `Wegpay requires jQuery ${minJQueryVersion}+, but you have ${window.jQuery.fn.jquery}`
    )
  }

  if (!hasJquery) await loadScript(jQuerySrc)
  if (!hasWegpay) await loadScript(iFrameJSsrc)
}

/**
 * Use this method to trigger the wegpay iframe to load, to get an iframe submitting function and to collect results
 *
 * 1. Calls submitFnGetter that you passed - giving you a submitting function that you can use to submit the iframe
 * 2. Triggers the load of the wegman's iframe in the passed targetDomId
 * 3. Return you a promise that will resolve/reject with the result from Wegpay
 * 4. Once the aforementioned submit function is called and the iframe submits, the returned promise will resolve/reject
 *
 * @param   {string}   targetDomId            - The DOM Id to inject the iframe into
 * @param   {object}   wegpayConfig           - An object containing wegpay config as returned by wegpay themselves
 * @param   {object}   lang                   - An object containing lang strings for wegpay
 * @param   {string}   transactionId          - The transaction Id (unique)
 * @param   {string}   orderId                - The order Id (unique)
 * @param   {boolean}  autoAdjustHeight       - If true, causes the iframe to adjust height automatically
 * @param   {function} submitFnGetter         - A function that is called to pass the submit function, to submit the iframe
 *
 * @return {Promise} callbackPromise           - Resolves to: res
 *  @resolves {Object} callbackPromise --> res     Response from Wegmans
 *  @rejects  {Error}  callbackPromise --> error   An error as reported by Wegman's
 */
export const showWegpayIframe = (
  targetDomId,
  wegpayConfig,
  lang,
  transactionId,
  orderId,
  autoAdjustHeight,
  submitFnGetter
) => {
  if (typeof window.jQuery === 'undefined' || typeof window.EprotectIframeClient === 'undefined') {
    // eslint-disable-next-line prefer-promise-reject-errors
    return Promise.reject(
      'Missing jQuery and/or Wegpay client js file. Call "importWegpayDependencies"!'
    )
  }

  let eProtectIframeClient = null
  let promiseFns = null

  submitFnGetter((isDoSubmit, iframeOpts = null) => {
    const opts = iframeOpts || {
      id: transactionId,
      orderId,
      pciNonSensitive: false,
    }

    if (isDoSubmit === false) {
      return promiseFns && promiseFns.reject(new Error('Add card was cancelled'))
    }

    eProtectIframeClient.getPaypageRegistrationId(opts)
  })

  return new Promise((resolve, reject) => {
    const config = {
      div: targetDomId,
      paypageId: wegpayConfig.payPageId,
      reportGroup: wegpayConfig.payPageReportGroup,
      showCvv: true,
      style: wegpayConfig.iFrameStyleSheet,
      timeout: 15000, // Recommended by Wegpay, 15 seconds
      htmlTimeout: 60000,
      numYears: 10, // Expiry needs '20' at the front when using Wegpay 'numYears' option
      tabIndex: {
        accountNumber: 1,
        cvv: 2,
        expMonth: 3,
        expYear: 4,
      },
      callback: res => (res && res.response === WEGPAY_SUCCESS ? resolve(res) : reject(res)),
      ...lang,
      ...(!autoAdjustHeight ? { height: '100%' } : {}),
    }

    eProtectIframeClient = new window.EprotectIframeClient(config)

    if (autoAdjustHeight) {
      eProtectIframeClient.autoAdjustHeight()
    }

    promiseFns = { resolve, reject }
  }).then(res => {
    if (!res || !res.paypageRegistrationId) {
      throw new Error('Wegpay response is missing or missing ext_id (paypageRegistrationId)')
    }

    return res
  })
}
