import angular from 'angular'

import { isRxProduct } from '@instacart/enterprise-services/product/utils'

export default angular
  .module('imageURLServicesModule', [])
  .service('imageURLService', function imageURLService(utilService) {
    const service = this

    service.getProductImageURL = (product, size) => {
      if (isRxProduct(product)) {
        return require('static/images/rx-default-image.png')
      }
      // We need this here incase we get a cart item or a store product
      const storeProduct = product?.store_product ? product.store_product : product
      if (storeProduct) {
        if (storeProduct.images.tile[size] === null) {
          return require('static/images/no-product-image.jpg')
        }
        return utilService.escapeImageUrl(storeProduct.images.tile[size])
      }
    }

    // TODO - Deprecate getProductImageURL function or make the images property configurable.
    // This is for multiple product images
    service.getProductImageURLFromArray = (product = {}, size) => {
      if (product.tile && product.tile[size]) {
        return utilService.escapeImageUrl(product.tile[size])
      }
      return require('static/images/no-product-image.jpg')
    }

    const getDefaultRecipeImageURL = shape => {
      if (shape === 'square') {
        return require('static/images/no-recipe-image-square.png')
      }
      if (shape === 'rectangle') {
        return require('static/images/no-recipe-image-rectangle.png')
      }
      return null
    }

    service.getRecipeImageURL = (recipe, shape) => {
      const recipeImageURL =
        recipe &&
        recipe.media &&
        recipe.media[0] &&
        recipe.media[0].image &&
        recipe.media[0].image.header

      return recipeImageURL
        ? utilService.escapeImageUrl(recipeImageURL)
        : getDefaultRecipeImageURL(shape)
    }

    service.getRecipeCategoryImageURL = recipeCategory => {
      const recipeCategoryImageURL =
        recipeCategory &&
        recipeCategory.images &&
        recipeCategory.images.tile &&
        recipeCategory.images.tile.medium

      return recipeCategoryImageURL
        ? utilService.escapeImageUrl(recipeCategoryImageURL)
        : getDefaultRecipeImageURL('square')
    }

    service.getSearchImageUrl = () => {
      return require('static/images/no-product-image.jpg')
    }
  })
