import React, { memo, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { createStyles } from '@instacart/cocktail'

const useStyles = createStyles(() => ({
  iframe: {
    display: 'none',
    width: '100%',
    height: 240,
    textAlign: 'center',
  },
  iframeActive: {
    display: 'block',
  },
}))

const defaultLang = {
  iframeTitle: 'Credit card view',
}

const PaymentIframe = props => {
  const { id, i18n, onPaymentIframeRendered, showIframe, url } = props
  const [iframeRef] = useState(() => React.createRef())
  const styles = useStyles(props)

  useEffect(() => {
    onPaymentIframeRendered(iframeRef.current)
  }, [onPaymentIframeRendered, iframeRef])

  const lang = { ...defaultLang, ...i18n }

  return (
    <iframe
      id={id}
      ref={iframeRef}
      src={url}
      title={lang.iframeTitle}
      scrolling="auto"
      frameBorder="0"
      css={[styles.iframe, showIframe && styles.iframeActive]}
    />
  )
}

PaymentIframe.defaultProps = {
  onPaymentIframeRendered: () => {},
  showIframe: false,
  url: '',
}

PaymentIframe.propTypes = {
  /** Language object for overrides */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Id for this component - required */
  id: PropTypes.string.isRequired,
  /** Once the iframe is available: onPaymentIframeRendered(iframe) */
  onPaymentIframeRendered: PropTypes.func,
  /** Whether to show the iframe */
  showIframe: PropTypes.bool,
  /** The Vantiv Url to load (can be left blank not to load the iframe) */
  url: PropTypes.string,
}

export default memo(PaymentIframe)
