/* eslint-disable max-lines */

import _ from 'lodash'
import angular from 'angular'
import { CONTENT_ELEMENT } from '@app/angular/constants/elementSelectors'
import { COUNTRIES, COUNTRY_DIAL_CODES } from '@app/angular/constants/address'
import urlParser from '@app/utils/urlParser'
import ConfigurableProductServiceModule from '../../components/productDetails/configurableProduct/configurableProduct.service'
import { DEVICE_TYPES } from './mobile'

const LOCAL_STORE_MAX_AGE_MS = 1000 * 60 * 10 // 10 minutes maximum age

export default angular
  .module('utilServicesModule', [ConfigurableProductServiceModule.name])
  // https://stackoverflow.com/questions/29892353/angular-ui-router-resolve-state-from-url/30926025#30926025
  // Needed for getStateFromPath below.
  .config($stateProvider => {
    $stateProvider.decorator('parent', (internalStateObj, parentFn) => {
      // This fn is called by StateBuilder each time a state is registered

      // The first arg is the internal state. Capture it and add an accessor to public state object.
      internalStateObj.self.$$state = function () {
        return internalStateObj
      }

      // pass through to default .parent() function
      return parentFn(internalStateObj)
    })
  })
  .service(
    'utilService',
    function utilService(
      $controller,
      $document,
      $injector,
      $interval,
      $location,
      $ocLazyLoad,
      $q,
      $rootScope,
      $state,
      $stateParams,
      $timeout,
      $window,
      config,
      localStorageService,
      mobileService,
      promoCodeService
    ) {
      const service = this

      // Methods for determining the environment
      const { mode } = config.env
      service.isProd = () => mode === 'prod' || mode === 'prd'
      service.isUat = () => mode === 'uat'
      service.isDev = () => mode === 'dev'

      // Analytics helper functions
      const wrapArray = val => {
        return Array.isArray(val) ? val : val ? [val] : undefined
      }

      const normalizeName = str => {
        if (typeof str === 'string') {
          return str.toLowerCase()
        }

        return str
      }

      service.filterParams = (categoryNameFromId, { tags, category_ids, brand_names }) => {
        let categories = wrapArray(category_ids)
        if (categories) {
          // Transform this into normalized category names
          categories = categories.map(categoryNameFromId).map(normalizeName)
        }

        tags = wrapArray(tags)
        if (tags) {
          tags = tags.map(normalizeName)
        }

        brand_names = wrapArray(brand_names)
        if (brand_names) {
          brand_names = brand_names.map(normalizeName)
        }

        return {
          tags,
          categories,
          brand_names,
        }
      }

      // Scroll the page to a given position
      service.scrollToPosition = function (position) {
        if (mobileService.isMobile) {
          // Scroll the `content` element if mobile
          const content = $document[0].querySelector(CONTENT_ELEMENT)
          if (content) {
            content.scrollTop = position
          }
        } else {
          // Otherwise, scroll the body
          $document[0].body.scrollTop = position
          $document[0].documentElement.scrollTop = position
        }

        $timeout(() => $rootScope.$broadcast('scrollPositionChanged'))
      }

      // Scroll the page back to the top

      service.scrollToTop = function () {
        service.scrollToPosition(0)
      }

      // Scroll a given element to the top

      service.scrollElementToTop = function (targetElement) {
        if (!targetElement) {
          return
        }

        targetElement.scrollTop = 0
      }

      /**
       * Loads files from the `frontend-config/<CLIENT>/static` folder, or
       * app/static/images if it does not exist
       * @param { string } path - the image path
       */
      service.loadAssetImage = function (path) {
        try {
          // eslint-disable-next-line import/no-dynamic-require
          return require(`assets/${path}`)
        } catch {
          // Fall back to the app/static/images folder if image didn't exist
          return service.loadStaticImage(path.replace(/^(images\/)/, ''))
        }
      }

      /**
       * Loads images in the `app/static/images` folder
       * @param { string } path - the image path
       */
      service.loadStaticImage = path => {
        // eslint-disable-next-line import/no-dynamic-require
        return require(`static/images/${path}`)
      }

      service.loadInternalExternalAssets = path => {
        if (path.match(/^(\/\/|https?).*/)) {
          return path
        }

        return service.loadAssetImage(path)
      }

      service.getLogoFromBannerSlug = bannerSlug => {
        const banner =
          config.multipleBanners &&
          config.multipleBanners.banners &&
          config.multipleBanners.banners.find(b => b.bannerSlug === bannerSlug)
        const imageUrl = banner && banner.logoImageUrl
        return imageUrl ? service.loadAssetImage(imageUrl) : null
      }

      // Scroll specified element into view
      service.scrollIntoView = function (targetElement, containerSelector) {
        if (!targetElement) {
          return
        }

        let position = targetElement.offsetTop

        // If on mobile where we scroll a `content` element and if the target
        // element matches the content element, set the scroll to position to 0
        if (mobileService.isMobile) {
          const contentElement = document.querySelector(CONTENT_ELEMENT)

          if (contentElement === targetElement) {
            position = 0
          }
        }

        if (containerSelector) {
          // If a container selector is provided, scroll to that
          const containerElement = document.querySelector(containerSelector)

          if (containerElement) {
            containerElement.scrollTop = position
          }
        } else {
          // Otherwise scroll app to the top
          service.scrollToPosition(position)
        }
      }

      // This is needed here to prevent circular imports
      service.isMobileApp = () => {
        return mobileService.isMobileApp()
      }

      service.getPrependHashIfNeeded = function (value) {
        // Prepend '#' when html5 mode isn't enabled (cordova)
        if (value && value.startsWith('/') && !$location.$$html5) {
          return `#${value}`
        }
        return value
      }

      // check that link is an internal url
      service.isLinkInternal = function (url) {
        if (!url) {
          return true
        }

        const urlHost = urlParser(url).host
        return (
          !urlHost ||
          urlHost === $window.location.host ||
          (service.isMobileApp() &&
            config.env.webUrl &&
            urlHost === urlParser(config.env.webUrl).host)
        )
      }

      service.isLinkCurrentPageByUrl = (windowContext, link) => {
        if (windowContext.location.href === link) {
          return true
        }
        if (
          service.isLinkInternal(link) &&
          urlParser(windowContext.location.href).pathname === link
        ) {
          return true
        }
        return false
      }

      // open link outside of the app
      service.openExtLink = function (link) {
        // handle special condition where URL is to a video sharing site
        const videoModalService = $injector.get('videoModalService')
        if (videoModalService && videoModalService.isVideoURL(link)) {
          videoModalService.open(link)
        } else {
          window.open(link, '_blank')
        }
      }

      function LoadScriptError(message = 'LoadScriptError', info = {}) {
        Object.assign(this, { message }, info)
        this.name = 'LoadScriptError'
        this.stack = new Error().stack
      }
      LoadScriptError.prototype = new Error()

      service.scriptLoaded = {}

      service.loadScript = (path, opts = {}, timeout = 30000) => {
        // Prevent loading duplicate script
        if (service.scriptLoaded[path]) {
          return Promise.resolve()
        }

        return new Promise((resolve, reject) => {
          const script = document.createElement('script')

          Object.assign(script, opts)

          script.type = 'text/javascript'
          script.src = path
          script.async = true
          script.onload = () => {
            service.scriptLoaded[path] = true
            resolve()
          }
          script.onreadystatechange = () => {
            service.scriptLoaded[path] = true
            resolve()
          }
          script.onerror = () =>
            reject(new LoadScriptError('Script failed to load', { path, opts }))
          $document[0].body.appendChild(script)

          if (timeout) {
            $timeout(() => reject(new LoadScriptError('Script timed out', { path, opts })), timeout)
          }
        })
      }

      const STRIP_OUT_LEADING_ZEROS_REGEXP = /(\.0+)+$/
      service.semVerDifference = (a, b) => {
        let i
        let diff
        const segmentsA = a.replace(STRIP_OUT_LEADING_ZEROS_REGEXP, '').split('.')
        const segmentsB = b.replace(STRIP_OUT_LEADING_ZEROS_REGEXP, '').split('.')
        const l = Math.min(segmentsA.length, segmentsB.length)

        // eslint-disable-next-line no-plusplus
        for (i = 0; i < l; i++) {
          diff = parseInt(segmentsA[i], 10) - parseInt(segmentsB[i], 10)
          if (diff) {
            return diff
          }
        }
        return segmentsA.length - segmentsB.length
      }

      // FIXME: This is a terrible hack

      service.repaint = function () {
        $timeout(() => {
          document.body.style.display = 'none'
          // eslint-disable-next-line no-unused-expressions
          document.body.offsetHeight // jshint ignore:line
          document.body.style.display = 'block'
        }, 100)
      }

      // Safe wrapper for console.log

      service.log = function (...args) {
        // eslint-disable-next-line no-console
        if (console && console.log) {
          // eslint-disable-next-line no-console
          console.log(...args)
        }
      }

      // Focus elements matched by the given selector

      service.focusSelector = function (selector, element) {
        const parent = element || document
        const els = parent.querySelectorAll(selector)
        $timeout(() => {
          _.each(els, el => {
            el.focus()
          })
        })
      }

      service.focusNodeWhenInDOM = function (selector, element, maxTries = 10) {
        const parent = element || document
        const waitFocus = $interval(
          () => {
            const els = parent.querySelectorAll(selector)
            if (els.length) {
              // focus on first element
              els[0].focus()
            }
          },
          100,
          maxTries
        ).then(function () {
          // destory interval after max tries
          $interval.cancel(waitFocus)
        })
      }

      // Collapse the keyboard on mobile devices by blurring the in-focus element

      service.blurAndHideKeyboard = function (isBlurOfIframe) {
        if (isBlurOfIframe && mobileService.getMobileOs() === DEVICE_TYPES.IOS) {
          // Cannot blur a text input in an iframe - have to create a new input and blur it to force
          // a blur event to occur, which then hides the keyboard
          const focusable = document.createElement('input')
          focusable.setAttribute('type', 'text')
          focusable.setAttribute('class', 'ios-focusable-input-fix')
          document.body.appendChild(focusable)
          focusable.onblur = () => document.body.removeChild(focusable)
          focusable.onfocus = () => focusable.blur()
          focusable.focus()
        } else {
          document.activeElement.blur()
        }
      }

      // Escape image urls to prevent issues with spaces and other
      // special characters (additionally using regex to replace parentheses if `escapeParentheses` is true)
      // Typically, `escapeParentheses` would be required for background-image URLs
      service.escapeImageUrl = function (path, escapeParentheses) {
        let escaped = ''
        if (escapeParentheses) {
          escaped = encodeURI(path).replace(/(?=[()])/g, '\\')
        } else {
          escaped = encodeURI(path).replace("'", "\\'")
        }

        return escaped
      }

      // Less finicky version of $state.is, which ignores
      // parameters which are present in the current state
      // but aren't provided in `params`

      // Uses $state.current by default, or compareState if
      // one is provided.

      service.stateIs = function (stateName, params, compareStateName, compareParams) {
        let match = true
        if (!compareStateName) {
          compareStateName = $state.current.name
        }
        if (!compareParams) {
          compareParams = $stateParams
        }
        // eslint-disable-next-line eqeqeq
        if (compareStateName != stateName) {
          match = false
        }
        _.each(params, (param, key) => {
          // eslint-disable-next-line eqeqeq
          if (param && compareParams[key] != param) {
            match = false
          }
        })
        return match
      }

      // Make sure pagination and brand filters doesn't transfer between states
      // FIXME: Find a more general way of doing this throughout the app

      service.withoutPage = function (params) {
        if (params) {
          params = angular.copy(params)
          params.page = null
          params.brand_name = null
        } else {
          params = {
            page: null,
            brand_name: null,
          }
        }
        return params
      }

      // FIXME

      service.cleanShopParams = function (params) {
        if (params) {
          params = angular.copy(params)
          params.page = null
          params.brand_name = null
          params.sort = null
        } else {
          params = {
            page: null,
            brand_name: null,
            sort: null,
          }
        }
        return params
      }

      // Go back to previous state or home if there is none

      const previousStates = []
      let lastState = null
      let wentBack = false
      $rootScope.$on('$stateChangeStart', () => {
        const previousState = {
          name: $state.current.name,
          params: $state.params,
        }
        lastState = previousState
        if (!wentBack) {
          previousStates.push(previousState)
        }
        wentBack = false
      })

      service.goBack = function () {
        const previousState = previousStates.pop()
        if (previousState && previousState.name) {
          wentBack = true
          $state.go(previousState.name, previousState.params)
        } else {
          $state.go('main.home')
        }
      }

      service.getLastState = () => lastState

      // Fetch the provided image and return a base64 encoded representation

      service.getBase64Image = function (src) {
        const deferred = $q.defer()

        const img = new Image()
        img.crossOrigin = 'Anonymous'

        img.onload = function () {
          const canvas = document.createElement('CANVAS')
          const ctx = canvas.getContext('2d')
          canvas.height = this.height
          canvas.width = this.width
          ctx.drawImage(this, 0, 0)
          const dataURL = canvas.toDataURL('image/png')
          deferred.resolve(dataURL)
        }

        img.src = src

        if (img.complete || img.complete === undefined) {
          img.src = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///ywAAAAAAQABAAACAUwAOw=='
          img.src = src
        }

        img.onerror = deferred.reject.bind(deferred)
        return deferred.promise
      }

      // Returns an arbitrary array of the provided length.
      service.getArrayOfSpecificLength = num => {
        return new Array(num)
      }

      // deep search for javascript objects
      // return an array of objects according to key, value, or key and value matching
      // https://gist.github.com/YagoLopez/1c2fe87d255fc64d5f1bf6a920b67484
      service.getObjects = (obj, key, val) => {
        let objects = []
        // eslint-disable-next-line no-restricted-syntax
        for (const i in obj) {
          // eslint-disable-next-line no-prototype-builtins, no-continue
          if (!obj.hasOwnProperty(i)) continue
          if (typeof obj[i] === 'object') {
            objects = objects.concat(service.getObjects(obj[i], key, val))
            // eslint-disable-next-line eqeqeq
          } else if ((i == key && obj[i] == val) || (i == key && val == '')) {
            //
            objects.push(obj)
            // eslint-disable-next-line eqeqeq
          } else if (obj[i] == val && key == '') {
            // only add if the object is not already in the array
            // eslint-disable-next-line eqeqeq
            if (objects.lastIndexOf(obj) == -1) {
              objects.push(obj)
            }
          }
        }
        return objects
      }

      service.parseQueryParams = queryString => {
        if (!queryString) {
          return {}
        }
        const params = {}
        const kvs =
          queryString.indexOf('?') === 0 ? queryString.slice(1).split('&') : queryString.split('&')
        // eslint-disable-next-line no-plusplus
        for (let i = 0; i < kvs.length; i++) {
          const pair = kvs[i].split('=')
          params[decodeURIComponent(pair[0])] =
            pair.length > 1 ? decodeURIComponent(pair[1] || '') : true
        }
        return params
      }

      service.openInternalUrl = url => {
        const { pathname, search } = urlParser(url)
        $location.url(pathname + search)
        // $apply only if not already in $apply.
        if (!$rootScope.$$phase) {
          $rootScope.$apply()
        }
      }

      service.getStateFromPath = path => {
        // eslint-disable-next-line no-restricted-syntax
        for (const state of $state.get()) {
          if (state.$$state) {
            const privatePortion = state.$$state()
            const match = privatePortion.url.exec(path)
            if (match && !state.abstract) {
              return [state.name, match]
            }
          }
        }
      }

      service.getStateFromUrl = url => {
        const params = urlParser(url)
        if (params.host && params.host === urlParser(config.env.webUrl).host && !params.search) {
          return service.getStateFromPath(params.pathname)
        }
      }

      service.generateUrlQuery = data => {
        const parts = []
        // eslint-disable-next-line no-restricted-syntax, guard-for-in
        for (const d in data) {
          parts.push(`${encodeURIComponent(d)}=${encodeURIComponent(data[d])}`)
        }
        return parts.join('&')
      }

      // Example usage:
      // const addFooterPrefixedIdsToLinks = utilService.generateAddIdToSrefLinkWithPrefix('footer');
      // links = links_.map(footerLinks, addFooterIdTagsToLinks);
      service.generateAddIdToSrefLinkWithPrefix = (prefix = '') => link => {
        if (link.sref) {
          return {
            ...link,
            id: `${prefix ? `${prefix}-` : ''}${link.sref.replace(/\./g, '-')}`,
          }
        }
        return link
      }

      service.addDays = (startDate, numberOfDays) => {
        // function to add days to a given date.
        const returnDate = new Date(
          startDate.getFullYear(),
          startDate.getMonth(),
          startDate.getDate() + numberOfDays,
          startDate.getHours(),
          startDate.getMinutes(),
          startDate.getSeconds()
        )
        return returnDate
      }

      // Removes `paramsToRemove` from `toParams` and `$location.search()`, replaces current state in history
      // And goes to `toState` without changing the location (so params not specified in `toState` are retained)
      service.removeSearchParametersGoToState = (toState, toParams, paramsToRemove, goOptions) => {
        $location.search(_.omit($location.search(), paramsToRemove))
        $location.replace()
        $state.go(toState, _.omit(toParams, paramsToRemove), goOptions)
      }

      service.openPromoCodeModal = (
        onClose,
        newVersion = false,
        disableOfferModal,
        onSuccess = () => {}
      ) => {
        promoCodeService.open({
          isUserLoggedIn: false,
          onClose: () => {
            const params = $stateParams
            params.enterPromoCode = null
            $state.go($state.current.name, params, { notify: false })
            if (typeof onClose === 'function') {
              onClose()
            }
          },
          onSuccess,
          newVersion,
          disableOfferModal,
        })
      }

      service.isIE = () => !!window.MSInputMethodContext && !!document.documentMode

      // For address validation, function takes in two objects of string values and checks for differences
      // then outputs an object that contains values of the expected spelling
      service.stringDiff = (inputStrings, expectedStrings) => {
        const diff = _.mapValues(expectedStrings, (expectedString, key) => {
          const stringA = expectedString
            ? expectedString.toLocaleLowerCase().replace(/\./g, '')
            : null
          const stringB = inputStrings[key]
            ? inputStrings[key].toLocaleLowerCase().replace(/\./g, '')
            : null

          return {
            value: expectedString,
            changed: stringA !== stringB,
          }
        })

        return diff
      }

      // Checks if browser is compatible with `IntersectionObserver` API
      service.hasIntersectionObserver = () =>
        'IntersectionObserver' in window &&
        'IntersectionObserverEntry' in window &&
        'intersectionRatio' in window.IntersectionObserverEntry.prototype &&
        'isIntersecting' in window.IntersectionObserverEntry.prototype

      /* alphanum.js (C) Brian Huisman
       * Based on the Alphanum Algorithm by David Koelle
       * The Alphanum Algorithm is discussed at http://www.DaveKoelle.com
       *
       * Distributed under same license as original
       *
       * Released under the MIT License - https://opensource.org/licenses/MIT
       *
       * Permission is hereby granted, free of charge, to any person obtaining
       * a copy of this software and associated documentation files (the "Software"),
       * to deal in the Software without restriction, including without limitation
       * the rights to use, copy, modify, merge, publish, distribute, sublicense,
       * and/or sell copies of the Software, and to permit persons to whom the
       * Software is furnished to do so, subject to the following conditions:
       *
       * The above copyright notice and this permission notice shall be included
       * in all copies or substantial portions of the Software.
       *
       * THE SOFTWARE IS PROVIDED "AS IS", WITHOUT WARRANTY OF ANY KIND,
       * EXPRESS OR IMPLIED, INCLUDING BUT NOT LIMITED TO THE WARRANTIES OF
       * MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT.
       * IN NO EVENT SHALL THE AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM,
       * DAMAGES OR OTHER LIABILITY, WHETHER IN AN ACTION OF CONTRACT, TORT OR
       * OTHERWISE, ARISING FROM, OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE
       * USE OR OTHER DEALINGS IN THE SOFTWARE.
       */

      /* ********************************************************************
       * Alphanum sort() function version - case sensitive
       * Taken from http://www.davekoelle.com/files/alphanum.js with some es6 changes
       */
      service.naturalSortFunc = (a, b) => {
        const chunkify = t => {
          const tz = []
          let x = 0
          let y = -1
          let n = 0
          let i
          let j

          // eslint-disable-next-line no-cond-assign, no-plusplus
          while ((i = (j = t.charAt(x++)).charCodeAt(0))) {
            // eslint-disable-next-line eqeqeq
            const m = i == 46 || (i >= 48 && i <= 57)
            if (m !== n) {
              // eslint-disable-next-line no-plusplus
              tz[++y] = ''
              n = m
            }
            tz[y] += j
          }
          return tz
        }

        const aa = chunkify(a)
        const bb = chunkify(b)

        // eslint-disable-next-line no-plusplus
        for (let x = 0; aa[x] && bb[x]; x++) {
          if (aa[x] !== bb[x]) {
            const c = Number(aa[x])
            const d = Number(bb[x])
            // eslint-disable-next-line eqeqeq
            if (c == aa[x] && d == bb[x]) {
              return c - d
            }
            return aa[x] > bb[x] ? 1 : -1
          }
        }
        return aa.length - bb.length
      }

      /**
       * Gets a value from LocalStore if the value isn't considered expired - usually: LOCAL_STORE_MAX_AGE_MS
       * The contents stored should be of the format: "{ value: <anything>, time: <int:getTime()>, maxAge: <[int]> }"
       *
       * @param {string} storeKey - The store key to get the data from
       *
       * @returns {*|undefined}
       */
      service.getExpiringValueFromStore = storeKey => {
        try {
          const payload = localStorageService.deprecatedGet(storeKey)
          if (
            payload &&
            (payload.maxAge === null || new Date().getTime() - payload.time <= payload.maxAge)
          ) {
            return payload.value
          }
        } catch (e) {
          console.warn(e, { storeKey })
        }
      }

      /**
       * Sets a value to LocalStore, including an expiry time along with it - default: LOCAL_STORE_MAX_AGE_MS
       * The contents stored should be of the format: "{ value: <anything>, time: <int:getTime()>, maxAge: <[int]> }"
       * If the value is null, the key will be removed from the store
       *
       * @param {string}   storeKey - The store key to get the data from
       * @param {*|null}   value    - The value to store, or null to delete
       * @param {int|null} [maxAge] - The maximum age of the data in ms, or null for no expiry
       *
       * @returns {undefined}
       */
      service.setExpiringValueToStore = (storeKey, value, maxAge = LOCAL_STORE_MAX_AGE_MS) => {
        try {
          if (value !== null) {
            localStorageService.deprecatedSet(
              storeKey,
              JSON.stringify({ value, maxAge, time: new Date().getTime() })
            )
          } else {
            localStorageService.deprecatedRemove(storeKey)
          }
        } catch (e) {
          console.warn(e)
        }
      }

      /**
       * Whether fee is zero or not
       *
       * @param {Object} fee      - A fee config object
       * @return {Boolean}        - True if is zero
       */
      service.isZero = fee => {
        const value = parseFloat(fee.value)
        // eslint-disable-next-line no-restricted-globals
        return !isNaN(value) && value === 0
      }

      /**
       * Whether fee is above zero
       *
       * @param {Object} fee      - A fee config object
       * @return {Boolean}        - True if is > 0
       */
      service.isNonZero = fee => {
        const value = parseFloat(fee.value)
        // eslint-disable-next-line no-restricted-globals
        return !isNaN(value) && value > 0
      }

      /**
       * Returns if the dotCom header is present on mobile
       * @returns {boolean}
       */
      service.isMobileDotComHeaderPresent = () => {
        const dotComConfig = config.dotComModule
        const { isMobile, isMobileApp } = mobileService
        const isMobileNative = isMobile && isMobileApp()
        const isDotComEnabled = dotComConfig.enable && dotComConfig.enableHeaderFooterSync

        return (
          isDotComEnabled &&
          ((isMobileNative && dotComConfig.headerSyncUrl.mobileApp) ||
            (isMobile && dotComConfig.headerSyncUrl.mobileWeb) ||
            (!isMobile && dotComConfig.headerSyncUrl.desktop))
        )
      }

      /**
       * This method traps the browser focus inside a given *element*.
       * While the focus trap is active, the user won't be able to exit
       * the targeted element using keyboard navigation.
       *
       * @param { HTMLElement } element
       *
       * @returns { Function } - *handleTabNavigation* function - required for a
       * possible future removeEventListener call
       */
      service.trapFocusOn = (element, initialFocus) => {
        if (element && element.addEventListener) {
          if (initialFocus) {
            // Optionally set focus on the first element in target
            const initialFocusableElements = service.getFocusableElements(element)
            const { 0: firstFocusElement } = initialFocusableElements
            if (firstFocusElement && firstFocusElement.focus) {
              firstFocusElement.focus()
            }
          }

          const handleTabNavigation = event => {
            // get focusableElements and remove any that have tabindex=-1 added dynamically
            const focusableElements = service.getFocusableElements(element)
            const focusableElementsArray = Array.from(focusableElements).filter(
              el => el.tabIndex !== -1
            )
            const {
              0: firstElement,
              [focusableElementsArray.length - 1]: lastElement,
            } = focusableElementsArray
            const isTabPressed = event.which === 9 || [event.code, event.key].includes('Tab')

            if (!isTabPressed) return

            if (event.target === lastElement && !event.shiftKey) {
              if (firstElement && firstElement.focus) firstElement.focus()
              event.preventDefault()
              event.stopPropagation()
              return
            }

            if (event.target === firstElement && event.shiftKey) {
              if (lastElement && lastElement.focus) lastElement.focus()
              event.preventDefault()
              event.stopPropagation()
            }
          }

          element.addEventListener('keydown', handleTabNavigation)

          // The only known way to make elements outside of the target element unfocusable with
          // TalkBack is to add `aria-hidden="true"` to them. The code below leaves the target
          // element and its parents unaffected, but adds `aria-hidden="true"` to all other elements
          // on the page.
          const previousAriaHiddenValues = []
          const excludedElements = []
          let parentElement = element
          while (parentElement && parentElement !== document.body) {
            excludedElements.push(parentElement)
            parentElement = parentElement.parentElement
          }

          const elementQueue = []
          let currentElement = document.body
          while (currentElement) {
            if (currentElement !== document.body && !excludedElements.includes(currentElement)) {
              previousAriaHiddenValues.push([
                currentElement,
                currentElement.getAttribute('aria-hidden'),
              ])
              currentElement.setAttribute('aria-hidden', 'true')
            } else {
              ;[...currentElement.children]
                .filter(child => child.tagName !== 'SCRIPT' && child !== element)
                .forEach(element => elementQueue.push(element))
            }
            currentElement = elementQueue.shift()
          }

          return () => {
            element.removeEventListener('keydown', handleTabNavigation)

            previousAriaHiddenValues.forEach(([element, value]) => {
              if (value) {
                element.setAttribute('aria-hidden', value)
              } else {
                element.removeAttribute('aria-hidden')
              }
            })
          }
        }
      }

      /**
       * Returns a list of focusable elements in a given DOM *element*
       *
       * @param { HTMLElement } element
       * @returns { HTMLElement[] }
       */
      service.getFocusableElements = element =>
        element.querySelectorAll(`
        [href],
        button:not([disabled]),
        input:not([disabled]):not(readonly),
        select:not([disabled]):not(readonly),
        textarea:not([disabled]):not(readonly),
        [tabindex]:not([tabindex="-1"])
      `)

      /**
       * This method is used to focus within a given *element*
       * An *index* can be provided, or it will default to the last index within the *element* (i.e. in most cases, the previous element)
       *
       * @param { HTMLElement } element
       * @param { number } index
       */
      service.focusWithin = (element, index) => {
        const focusableElements = service.getFocusableElements(element)
        const focusIndex = typeof index !== 'undefined' ? index : focusableElements?.length - 1
        focusableElements[focusIndex].focus()
      }

      /*
       * Returns if the current application $state is using a fixed layout
       *
       * @returns { Boolean }
       */
      service.isFixedLayout = () => Object.keys($state.$current.views).includes('fixed-content@')

      /**
       * Returns a phone number with the global code
       *
       * @param { string } phoneNumber
       * @param { string} country
       *
       * @returns { string }
       */
      service.getInternationalPhoneNumber = (phoneNumber, country = COUNTRIES.USA) => {
        if (!phoneNumber) return ''

        return `${COUNTRY_DIAL_CODES[country]}${phoneNumber}`
      }

      /**
       * Helper function to lazy load angular js controllers!
       * Use it in routers to load controllers beforehand. Will only load them if not already loaded.
       *
       * @example
       *  resolve: {
       *    // Load dependencies
       *    $lazyLoadControllers: function(utilService) {
       *      return utilService.lazyLoadControllers({
       *        MyController: () =>  // NOTE: Key matches expected controller name
       *          import(  // NOTE: Import inside a function so it can be called only as needed
       *            /* webpackChunkName: "myController", webpackPrefetch: true *<remove this>/ './myController'
       *          ),
       *        // etc
       *      })
       *    },
       *  }
       *
       * @param {object} moduleFnsMap  - An object: Keys as controller names, values as functions
       *                                 calling import statements which return module promises
       * @returns {Promise<T>}         - Promise once controllers are loaded
       */
      service.lazyLoadControllers = moduleFnsMap => {
        return Promise.all(
          Object.entries(moduleFnsMap)
            .map(([controllerName, moduleFn]) =>
              !isController(controllerName) ? moduleFn() : null
            )
            .filter(promise => promise !== null)
        )
          .then(
            modules => modules.length > 0 && $ocLazyLoad.load(modules.map(module => module.default))
          )
          .catch(err => {
            throw new Error(`Ooops, something went wrong, ${err}`)
          })
      }

      /** @private Helper function to check if a controller is already loaded in Angular JS */
      function isController(controllerName) {
        try {
          if (typeof window[controllerName] === 'function') return true
          return !!$controller(controllerName)
        } catch (error) {
          return !(error instanceof TypeError)
        }
      }
    }
  )
  .run(($rootScope, $timeout, $uibModalStack) => {
    /*
     * This is a fix for modals overlapping each other - https://instacart.atlassian.net/browse/ECOM-337
     */
    $rootScope.$watch($uibModalStack.getTop, (newWin, oldWin) => {
      // Only if there is a new window and an old window - we need to fix the backdrop z-index!
      if (newWin && oldWin && newWin.value && newWin.value.modalDomEl) {
        $timeout(() => {
          const children = newWin.value.modalDomEl.children()
          const topWindowZIndexStr = children.length && children[0].style.zIndex
          const [modalBackdrop] = document.getElementsByClassName('modal-backdrop') || []

          // eslint-disable-next-line no-restricted-globals
          if (!isNaN(topWindowZIndexStr) && modalBackdrop) {
            modalBackdrop.style.zIndex = parseInt(topWindowZIndexStr, 10) - 1
          }
        })
      }
    })
  })
