import angular from 'angular'
import isCordova from '@app/utils/isCordova'

export default angular.module('codePushServicesModule', []).run(() => {
  if (isCordova()) {
    let isDownloadingUpdate = false

    const handleError = () => {
      isDownloadingUpdate = false
    }

    const downloadUpdate = function () {
      if (window.codePush) {
        if (isDownloadingUpdate) {
          return
        }

        isDownloadingUpdate = true

        window.codePush.checkForUpdate(remotePackage => {
          if (remotePackage) {
            remotePackage.download(localPackage => {
              localPackage.install(
                () => {
                  isDownloadingUpdate = false
                },
                handleError,
                {
                  // eslint-disable-next-line no-undef
                  installMode: InstallMode.ON_NEXT_RESTART,
                }
              )
            }, handleError)
          } else {
            isDownloadingUpdate = false
          }
        }, handleError)
      }
    }

    document.addEventListener('deviceready', () => {
      downloadUpdate()
    })

    document.addEventListener('resume', () => {
      downloadUpdate()
    })
  }
})
