import { createStyles } from '@app/react/common/hooks/createStyles'
import { useLang } from '@app/services/config/hooks'

interface UndoProps {
  itemName: string
  id?: string
  onUndo: () => void
}

const useStyles = createStyles(theme => ({
  button: {
    marginLeft: 10,
  },
  container: {
    alignItems: 'center',
    display: 'flex',
    fontSize: theme.typography.bodyMedium2.fontSize,
  },
}))

export default function UndoItemRemove({ itemName, onUndo, id }: UndoProps) {
  const lang = useLang()
  const styles = useStyles()
  return (
    <div css={styles.container} {...{ id }}>
      <div>
        {itemName} <b>{lang.undoItemRemoveToast.removed}</b>
      </div>
      <button className="button" type="button" css={styles.button} onClick={onUndo}>
        {lang.undoItemRemoveToast.undo}
      </button>
    </div>
  )
}
