import { FIRSTDATA_V2 } from '@app/angular/constants/paymentGateway'
import angular from 'angular'
import promiseable from '@app/utils/promiseable'
import {
  FIRST_DATA_CSS_CLASSES,
  FIRST_DATA_SELECTORS,
  FIRST_DATA_ERROR_TYPES,
  CARD_INPUT_ID,
  DESCRIPTION_INPUT_ID,
  ZIP_INPUT_ID,
} from '@app/angular/constants/firstDatav2'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'

export default angular
  .module('FirstDataIntegrationV2ControllerModule', [])
  .controller(
    'FirstDataIntegrationV2Controller',
    function (
      $filter,
      $q,
      $rootScope,
      $scope,
      $timeout,
      Restangular,
      config,
      errorModalService,
      firstDataV2Payment,
      lang,
      utilService
    ) {
      const vm = this
      let payeezyCallback

      const getDependencies = promiseable(async () => {
        const hasFirstDataJs = typeof window.firstdata !== 'undefined'
        if (!hasFirstDataJs) {
          await utilService.loadScript(FIRSTDATA_V2.JS_FILE_FN({ isProd: utilService.isProd() }))
        }
      })

      const onShowCardForm = $scope.onShowCardForm || $scope.$parent.onShowCardForm
      const checkoutNewCreditCard =
        $scope.checkoutNewCreditCard || $scope.$parent.checkoutNewCreditCard
      const { toggleIsShowingAddCard } = $scope
      const setCreditCard = () => $scope.doSave && $scope.doSave()

      vm.uiState = {
        selectedCard: null,
      }

      vm.error = ''
      vm.showAddCardForm = async () => {
        vm.error = ''
        vm.firstDataFormReady = false
        vm.showFirstDataForm = true
        vm.hideAddCardButton = true

        if (toggleIsShowingAddCard) toggleIsShowingAddCard(true)

        try {
          await Restangular.one('cart').one('payment').customPUT({ payment_type: 'auth_capture' })

          const { ext_data = {} } = await Restangular.one('payment').one('session').post()

          const { clientToken, publicKeyBase64 } = ext_data

          if (!clientToken || !publicKeyBase64) {
            throw new Error('Missing clientToken or publicKeyBase64!')
          }

          await $q.resolve(getDependencies())

          // Check that form is present before trying to show form
          // Happens if there is a redirect away from the page whilst resolving dependencies
          if (!_getDescriptionField() || !_getZipField()) {
            console.warn('No description field!')
            return
          }

          payeezyCallback = await initiateFirstData(
            lang.values.creditCardFields,
            clientToken,
            publicKeyBase64
          )

          if (onShowCardForm) {
            onShowCardForm()
          }

          $timeout(() => (vm.firstDataFormReady = true))
        } catch (error) {
          console.error('FirstData - Initialize error', error)
          vm.cancelAddCard()
          errorModalService.open(lang.values.defaultErrorMessage, lang.values.ok)
        }
      }

      /**
       * Fetches the payment instruments for v2
       *
       * @param {boolean} isNewOrUpdate - True if this is called due to a new card or updated card
       * @param {boolean} initialCall   - True if this is the initial call to getPaymentInstruments
       * @return {Object}               - Returns the selected payment instrument
       */
      vm.getPaymentInstruments = function ({ isNewOrUpdate = false, initialCall = false } = {}) {
        return Restangular.one('user')
          .one('payment')
          .get({ payment_type: firstDataV2Payment.information.key })
          .then(response => {
            const oldLength = vm.paymentInstruments?.length
            const instruments = response.auth_capture.payment_instruments
            const firstInstrument = instruments && instruments.length && instruments[0]
            vm.paymentInstruments = $filter('orderBy')(instruments, 'id', true)
            firstDataV2Payment.status.loaded = true

            const newLength = vm.paymentInstruments.length

            if (isNewOrUpdate) {
              vm.uiState.selectedCard = null

              if (newLength > oldLength) {
                vm.uiState.selectedCard = instruments.reduce((latest, instrument) => {
                  return new Date(latest.modified) - new Date(instrument.modified) < 0
                    ? instrument
                    : latest
                })
              }
            } else if (newLength > 0) {
              if (!vm.uiState.selectedCard) {
                // If nothing chosen, select first one by default
                vm.uiState.selectedCard = firstInstrument
              } else {
                // Make sure previously choice still exists
                const existing = vm.paymentInstruments.find(
                  instrument => instrument.id === vm.uiState.selectedCard?.ext_id
                )

                vm.uiState.selectedCard = existing || vm.paymentInstruments[0]
              }

              if (initialCall) {
                $timeout(() => setCreditCard())
              }
            } else {
              // Handle no payment methods existing
              vm.uiState.selectedCard = null
              vm.showAddCardForm()
            }

            return vm.uiState.selectedCard
          })
      }

      vm.uiState.selectedCard = $scope.payment?.auth_capture || null
      vm.getPaymentInstruments({ initialCall: true })

      vm.addCard = async function () {
        let success = null
        vm.error = ''
        vm.firstDataFormReady = false

        try {
          await payeezyCallback(validateDescriptionAndZip)

          vm.paymentListRefresh = true
          const { cardDescription: name, zipPostalCode: postal_code } = vm

          const instrument = await vm.getPaymentInstruments({ isNewOrUpdate: true })

          if (!instrument) {
            throw new Error('No new instrument was found for FirstDataV2')
          }

          instrument.name = name
          vm.paymentListRefresh = false

          const { id } = instrument
          await Restangular.one('user').one('payment', id).customPUT({ id, name, postal_code })

          success = true
        } catch (error) {
          if (error.status === HTTP_ERRORS.TOO_MANY_REQUESTS) {
            vm.error = lang.values.tooManyRetriesError
          } else if (error.FIRST_DATA_ERROR_TYPE === FIRST_DATA_ERROR_TYPES.VALIDATION) {
            vm.error = lang.values.creditCardFields.error
          } else {
            console.error('FirstData - Add card error', error)
            errorModalService.open(lang.values.firstDataV2.defaultErrorMessage, lang.values.ok)

            success = false
          }
        } finally {
          vm.firstDataFormReady = true

          if (success !== null) {
            vm.cancelAddCard()

            if (checkoutNewCreditCard) {
              checkoutNewCreditCard(success)
            }

            if (success) {
              $timeout(setCreditCard)
            } else {
              vm.getPaymentInstruments()
            }
          }
        }
      }

      vm.cancelAddCard = function () {
        vm.showFirstDataForm = false
        vm.hideAddCardButton = false
        vm.paymentListRefresh = false
        vm.cardDescription = ''
        vm.zipPostalCode = ''
        vm.error = ''

        if (toggleIsShowingAddCard) toggleIsShowingAddCard(false)
      }

      // Delete Credit Card
      vm.deleteCard = function (card) {
        const { confirm, cancel, deleteCard } = lang.values
        const errorServiceModalPromise = errorModalService.open(
          lang.templateAsString(lang.values.deleteCardMessage, {
            cardName: card.name,
            cardNumber: card.last_four_digits,
          }),
          confirm,
          cancel,
          deleteCard
        )

        vm.cancelAddCard()

        errorServiceModalPromise
          .then(() => Restangular.one('user/payment', card.id).remove())
          .then(() => vm.getPaymentInstruments())
      }

      // Allow user to proceed once a payment instrument has been selected
      $scope.$watch(
        () => vm.uiState.selectedCard,
        () => {
          if (vm.uiState.selectedCard) {
            $scope.payment[firstDataV2Payment.information.key] = {
              payment_instrument: vm.uiState.selectedCard,
            }
          }
          firstDataV2Payment.status.ready = !!vm.uiState.selectedCard
        }
      )
    }
  )

/**
 * Triggers the injection of iframes into appropriate fields.
 * For this to work, the following must be true:
 *  1. The appropriate div's with ids must be present: https://docs.paymentjs.firstdata.com/#library
 *  2. The `clientToken` and `publicKeyBase64` must be requested via the API to Payment JS
 *  3. The returned `onSubmitFn` should be captured and used when submitting the iframes
 *
 *
 * @param {Object} langStrings        - Object of lang strings: { cardHolderName, cardNumber, cvvCode, expiryDate, zipCode }
 * @param {String} clientToken        - Client token string
 * @param {String} publicKeyBase64    - Public key string
 *
 * @return {Promise<Function>}        - Promise resolving to a callback function, to be triggered
 *                                      when the submit button is pressed
 */
function initiateFirstData(langStrings, clientToken, publicKeyBase64) {
  // Create FirstData formConfig
  const { cardHolderName, cardNumber, cvvCode, expiryDateSingleField } = langStrings
  const { fontFamily, fontSize } = getComputedStyle(_getCardField()) || {}

  const formConfig = {
    styles: {
      input: {
        'font-family': fontFamily,
        'font-size': fontSize,
      },
    },

    classes: {
      empty: FIRST_DATA_CSS_CLASSES.EMPTY,
      focus: FIRST_DATA_CSS_CLASSES.FOCUS,
      invalid: FIRST_DATA_CSS_CLASSES.INVALID,
      valid: FIRST_DATA_CSS_CLASSES.VALID,
    },

    fields: {
      card: {
        selector: FIRST_DATA_SELECTORS.CARD,
        placeholder: cardNumber,
      },
      cvv: {
        selector: FIRST_DATA_SELECTORS.CVV,
        placeholder: cvvCode,
      },
      exp: {
        selector: FIRST_DATA_SELECTORS.EXP,
        placeholder: expiryDateSingleField,
      },
      name: {
        selector: FIRST_DATA_SELECTORS.NAME,
        placeholder: cardHolderName,
      },
    },
  }

  /**
   * Initialize FirstData form
   *
   * Returns a promise that resolves with a function which can be called to submit the FirstData
   * form. The function can be called with a preValidateFn to perform some pre-validation before
   * the payment form is submitted.
   */
  return new Promise((resolve, reject) => {
    const preFlowHook = providerFn => providerFn({ clientToken, publicKeyBase64 })
    const onReady = form => resolve(preValidateFn => _onSubmit(form, preValidateFn))

    try {
      window.firstdata.createPaymentForm(formConfig, { preFlowHook }, onReady)
    } catch (error) {
      reject(error)
    }
  })
}

/**
 * @private
 * Performs the iframe payeezy submission
 *
 * @param {Object} paymentForm        - The payeezy interface through which to submit the iframes
 * @param {Function} preValidateFn    - A pre-validation method
 *
 * @return {Promise<any>}
 */
function _onSubmit(paymentForm, preValidateFn) {
  return new Promise((resolve, reject) => {
    const validationError = error => {
      error.FIRST_DATA_ERROR_TYPE = FIRST_DATA_ERROR_TYPES.VALIDATION
      reject(error)
    }

    try {
      const isPreValid = preValidateFn()

      paymentForm.validate(() => {
        if (!isPreValid) {
          validationError(new Error('missing description / zip'))
          return
        }

        paymentForm.authenticate(
          auth => {
            paymentForm.submit(auth, resolve, error => {
              error.FIRST_DATA_ERROR_TYPE = FIRST_DATA_ERROR_TYPES.SUBMISSION
              reject(error)
            })
          },
          error => {
            error.FIRST_DATA_ERROR_TYPE = FIRST_DATA_ERROR_TYPES.AUTHENTICATION
            reject(error)
          }
        )
      }, validationError)
    } catch (error) {
      reject(error)
    }
  })
}

function validateDescriptionAndZip() {
  // Validate the separate boxes
  const descriptionInput = _getDescriptionField()
  const zipInput = _getZipField()
  descriptionInput.focus()
  descriptionInput.blur()
  zipInput.focus()
  zipInput.blur()
  return descriptionInput.value !== '' && zipInput.value !== ''
}

function _getDescriptionField() {
  return document.getElementById(DESCRIPTION_INPUT_ID)
}
function _getZipField() {
  return document.getElementById(ZIP_INPUT_ID)
}
function _getCardField() {
  return document.getElementById(CARD_INPUT_ID)
}
