import angular from 'angular'
import FacetedFilterModalLineItemModule from './facetedFilterModalLineItem.js'

export default angular
  .module('FacetedFilterModalSectionModule', [FacetedFilterModalLineItemModule.name])
  .component('facetedFilterModalSection', {
    bindings: {
      facet: '<',
    },
    controller() {},
    template: `
      <section ng-if="$ctrl.facet.group_display_name !== 'Categories'">
        <h2>{{::$ctrl.facet.group_display_name}}</h2>
        <ul class="tags list">
          <li ng-repeat="tagOption in $ctrl.facet.items" class="tag-option tag {{tagOption.value}} list-item" analytics-click="['tag filter ' + tagOption.value, 'click']">
            <faceted-filter-modal-line-item tag-option="tagOption"></faceted-filter-modal-line-item>
          </li>
        </ul>
      </section>
    `,
  })
