import angular from 'angular'
import template from './paypal.html'
import templateCheckout from './paypalCheckout.html'

const REDIRECT_KEY = 'paypalErrorRedirect'

export default angular
  .module('PayPalGatewayComponentModule', [])
  .config(($stateProvider, paymentGatewayProvider) => {
    paymentGatewayProvider.registerGateway({
      name: 'PayPal',
      key: 'paypal',
      template: templateCheckout,
      serviceName: 'paypalPayment',
      noSettingsRequired: true,
      newCheckoutCssClass: 'provider-generic_styling',
    })

    $stateProvider.state('main.checkout.paypal', {
      url: '/paypal?session-token&paymentId&PayerID&token',
      views: {
        checkout: {
          template,
        },
      },
      onEnter(
        $rootScope,
        $state,
        $timeout,
        $window,
        addressList,
        analytics,
        auth,
        checkoutAnalytics,
        config,
        errorModalService,
        localStorageService,
        Restangular
      ) {
        $timeout(() => {
          // Utility function to handle any of the various errors that can occur in this process

          const handleError = function (response) {
            errorModalService
              .open(response.data ? response.data.message : response.message, 'OK')
              .finally(() => {
                const state = localStorageService.deprecatedGet(REDIRECT_KEY)
                $state.go(state || 'main.checkout.payment')
              })

            checkoutAnalytics.placeOrderClick({ order: null, success: false })
          }

          let paymentId = window.location.href.match(/paymentId=([\w-]+)/)
          if (paymentId) {
            paymentId = paymentId[1]
          }

          let payerId = window.location.href.match(/PayerID=([\w]+)/)
          if (payerId) {
            payerId = payerId[1]
          }

          const ordersParams = {
            payment_type: 'paypal',
            paypal: {
              payment_id: paymentId,
              payer_id: payerId,
              return_url: $window.location.href,
            },
          }
          if (config.enableMultipleDeliveryAddresses) {
            const context = auth.getContext()
            if (context.intent === 'delivery') {
              ordersParams.delivery_address = addressList.getAddressFromId(context.address_id)
            }
          }
          Restangular.one('orders')
            .post(null, ordersParams)
            .then(order => {
              // don't call analytics on a modified order
              if (!$rootScope.modifyingOrder) {
                analytics.logCheckoutStep(5, 'paypal', order.order_items)
                analytics.logCheckoutCompletion(order)
                analytics.logCriteoOrderConfirmation({
                  id: order.id,
                  orderItems: order.order_items,
                })

                checkoutAnalytics.placeOrderClick({ order, success: true })
                analytics.logFBPurchase(order)
              }
              $state.go('main.checkout.confirmation', { orderId: order.id }, { reload: true })
            }, handleError) // If checkout is unsuccessful, show an error
        })
      },
    })
  })

  .service('paypalPayment', function paypalPayment($q, $state, $window, localStorageService) {
    const service = this

    service.status = { loaded: true, ready: true }

    service.checkout = function (payment, response) {
      const deferred = $q.defer()

      localStorageService.deprecatedSet(REDIRECT_KEY, $state.current.name)
      // In checkout v2 the paypal object is on response.response
      payment.paypal = response.paypal || response.response.paypal // Update paypal URL
      $window.location = payment.paypal.approval_url

      return deferred.promise
    }
  })
