// Updates the model of the form element when enter is pressed
// and removes focus.

import angular from 'angular'

export default angular
  .module('updateOnEnterComponentModule', [])
  .directive('updateOnEnter', function () {
    return {
      restrict: 'A',
      link(scope, el) {
        el.bind('keydown keypress', event => {
          if (event.which === 13) {
            scope.$apply()
            el[0].blur()
          }
        })
      },
    }
  })
