import angular from 'angular'

import InstacartCustomerLinkModalServiceModule from './instacartCustomerLinkModalService'

export default angular
  .module('InstaCartCustomerLinkComponentModule', [InstacartCustomerLinkModalServiceModule.name])
  .config($stateProvider => {
    $stateProvider.state('main.linkaccount', {
      url: '/link-account',
      onEnter: ($state, instacartCustomerLinkModalService) => {
        $state.go('main.home').then(() => {
          instacartCustomerLinkModalService.openModal({ startStage: 'link' })
        })
      },
      data: {
        requireRegistered: true,
      },
    })
  })
