import angular from 'angular'

export default angular.module('TagStyleModule', []).directive('tagStyle', function () {
  return {
    scope: {
      tag: '<',
      tagLocation: '@',
    },
    controller($scope, tagsService) {
      const getTagValues = (tagValue, location) => tagsService.byVal(tagValue, location)
      if (typeof $scope.tag === 'string') {
        $scope.tagObj = getTagValues($scope.tag, $scope.tagLocation)
      } else {
        $scope.tagObj = $scope.tag
      }
    },
    link(scope, el) {
      const tag = scope.tagObj
      const tagLocation = scope.tagLocation

      if (!tag) {
        // no tag, no styling
        el.addClass('hide-empty')
        return
      }
      // because the default value for circle is assumed to be true, we need to check if the
      // config value has been explicitly set to false
      // if no background color is set, we default to white
      const defaultBackColor = '#FFFFFF'

      if (tag.circle === false) {
        el.css('color', tag.color)
        el.css('backgroundColor', tag.backgroundColor || defaultBackColor)
      } else {
        el.css('background-color', tag.color)
        el.css('color', tag.backgroundColor || defaultBackColor)
      }

      if (tag.fontSize) {
        el.css('font-size', tag.fontSize)
      }

      if (tag.icon) {
        if (tagLocation === 'mobile-home') {
          el.append(
            `<span>
              <span class="icon-${tag.icon}" aria-hidden="true" />
            </span>`
          )
        } else {
          el.append(
            `<span>
              <span class="screen-reader-text">${tag.name}</span>
              <span class="icon-${tag.icon}" aria-hidden="true" />
            </span>`
          )
        }
      } else {
        // hide the icon
        el.addClass('hide-empty')
      }
    },
  }
})
