import angular from 'angular'
import dayjs from 'dayjs'
import * as storage from '@app/utils/storage'
import template from './mobileOnboardingModal.html'
import MobileOnboardingModalControllerModule from './mobileOnboardingModalController'

export default angular
  .module('MobileOnboardingServiceComponentModule', [MobileOnboardingModalControllerModule.name])
  // Service to show app onboarding carousel

  .service(
    'mobileOnboardingService',
    function mobileOnboardingService(
      $uibModal,
      analytics,
      auth,
      config,
      lang,
      mobileService,
      uibModalAriaLabelService
    ) {
      const service = this

      const storageService = storage.getInstance()

      function setLocalStorage() {
        // set localstorage to dismiss onboarding
        service.shouldShowOnboarding().then(showOnboarding => {
          if (showOnboarding) {
            storageService.setItem('dismissMobileOnboarding', config.mobileOnboarding.effectiveDate)
          }
        })
      }

      service.openModal = function () {
        return $uibModal
          .open({
            template,
            controller: 'MobileOnboardingModalController',
            keyboard: true,
            backdrop: 'static',
            resolve: {
              ariaLabel: () => {
                uibModalAriaLabelService.setLabel(lang.values.welcome)
              },
            },
          })
          .result.then(
            () => {
              setLocalStorage()
              analytics.logSegmentEvent('mobile.view_onboarding', {
                success: true,
              })
            },
            () => {
              setLocalStorage()
            }
          )
      }

      const userLoggedIn = auth.user && auth.user.status !== 'trial'
      const isMobileApp = mobileService.isMobileApp() // We only ever show mobile onboarding in the app, not web mobile
      const isMobileOnboardingEnabled =
        config.mobileOnboarding && config.mobileOnboarding.slides.length > 0
      // Checks if onboarding has been set for the retailer,
      // has not yet been dismissed in Local Storage and if current date is before onboarding expiry.
      // Also checks that user is not logged in as a fail safe (ENT-29421) as onboarding is for fresh installs only.
      service.shouldShowOnboarding = async function () {
        return storageService.getItem('dismissMobileOnboarding').then(storedValue => {
          if (isMobileApp && !userLoggedIn && isMobileOnboardingEnabled) {
            if (!storedValue) {
              const pastEffectiveDate = dayjs().isAfter(config.mobileOnboarding.effectiveDate)
              return pastEffectiveDate
            }
            const shouldViewNewOnboarding = dayjs(storedValue).isBefore(
              config.mobileOnboarding.effectiveDate
            )
            return shouldViewNewOnboarding
          }
          return false
        })
      }
    }
  )
