import { auth as enterpriseAuthService } from '@instacart/enterprise-services'
import { captureException } from '@sentry/browser'
import sleep from './sleep'

const validFileTypes = ['script', 'style']

export const isValidFileType = ({ type }: { type: string }): boolean =>
  validFileTypes.includes(type)

export const getLinkType = (path: string): string => {
  const fragments = path.split('.')
  const ending = fragments[fragments.length - 1]

  if (ending === 'js') {
    return 'script'
  }

  if (ending === 'css') {
    return 'style'
  }

  return ''
}

export const getFilesFromBundle = (bundle: string[]): { type: string; path: string }[] =>
  Object.values(bundle)
    .map(path => ({
      path,
      type: getLinkType(path),
    }))
    .filter(isValidFileType)

async function preloadCheckoutPage(): Promise<void> {
  // Sleep is to allow the page to calm down before injecting all the asset bundle files
  // This is because there isn't a good hook to use to determine when the app is in a calm state
  await sleep(2000)

  try {
    const bundleFile = await enterpriseAuthService.api.auth.getOneCartCheckoutAppBundle()

    const checkoutAssetFiles = getFilesFromBundle(bundleFile.assetPaths)

    const fragment = document.createDocumentFragment()

    checkoutAssetFiles.forEach(({ path, type }) => {
      const link = document.createElement('link')
      link.rel = 'preload'
      link.href = path
      link.as = type

      fragment.appendChild(link)
    })

    document.head.appendChild(fragment)
  } catch (e) {
    captureException(e)
  }
}

export default preloadCheckoutPage
