import angular from 'angular'

export default angular
  .module('BarcodeScannerButtonModule', [])
  .directive('barcodeScannerButton', function ($state, Restangular, barcodeScanner) {
    return function (scope, element) {
      if (barcodeScanner.isSupported()) {
        element.on('click', () => {
          $state.go('main.scanBarcode')
        })
      } else {
        element.remove()
      }
    }
  })
