import angular from 'angular'

export default angular
  .module('ReservationControllerModule', [])
  .controller(
    'ReservationController',
    function ($scope, $state, Restangular, auth, cartService, config, reservationService) {
      $scope.auth = auth
      $scope.cartService = cartService
      $scope.reservationService = reservationService
      $scope.state = $state
      $scope.shoppingIntent = auth.user.context.intent
    }
  )
