import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'

const mapHasPickup = require('static/images/map-pin-hasPickup.png')
const mapPinGrey = require('static/images/map-pin-grey.png')
const mapPinActive = require('static/images/map-pin-active.png')

export default angular
  .module('StoreSelectorMapControllerComponentModule', [])
  .controller(
    'StoreSelectorMapController',
    function StoreSelectorMapController(
      $filter,
      $http,
      $q,
      $scope,
      $timeout,
      auth,
      config,
      errorModalService,
      mobileService,
      shoppingContextService,
      storesService
    ) {
      const vm = this

      // Expose to views
      vm.config = config
      vm.mobile = mobileService
      vm.shopContextIntent =
        shoppingContextService.selectedFulfillmentType ||
        auth.getContext().intent ||
        config.defaultShoppingContext
      vm.distanceUnits = config.useKMForStoreSelectorUnits ? 'kilometers' : 'miles'
      vm.distanceUnitsAbbreviation = config.useKMForStoreSelectorUnits ? 'km' : 'm'

      vm.isStoreSelectable = store =>
        (store.show_pickup && vm.shopContextIntent === SHOPPING_MODES.PICKUP) ||
        (store.show_catering && vm.shopContextIntent === SHOPPING_MODES.CATERING)

      if (
        config.shoppingSelector[vm.shopContextIntent] &&
        config.shoppingSelector[vm.shopContextIntent].fulfilledByInstacart &&
        config.shoppingSelector[vm.shopContextIntent].poweredByTitle
      ) {
        vm.shoppingContextText = config.shoppingSelector[vm.shopContextIntent].poweredByTitle
      } else {
        vm.shoppingContextText =
          config.shoppingSelector[vm.shopContextIntent].shortTitle ||
          config.shoppingSelector[vm.shopContextIntent].title
      }

      vm.shoppingContextText =
        config.shoppingSelector[vm.shopContextIntent].title ||
        shoppingContextService.getShoppingModeTitle(vm.shopContextIntent)
      vm.showMap = !config.hideStoreSelectorMapOnInit
      vm.storeFilters = {
        banner_slug: '',
      }

      // Only show stores of primary banner if nonTabulatedView is enabled (ie. only show grocery for CUS)
      if (config.multipleBanners.enabled && config.multipleBanners.enableNonTabulatedView) {
        vm.storeFilters.banner_slug = config.multipleBanners.banners[0]?.bannerSlug ?? ''
      }

      if (vm.shopContextIntent === SHOPPING_MODES.PICKUP) {
        vm.storeFilters.onlyShowPickup = true
      }
      vm.stores = storesService.stores || null

      vm.storeFilterFn = value => {
        return (
          (vm.storeFilters.onlyShowPickup !== true || value?.show_pickup === true) &&
          (!vm.storeFilters.banner_slug || vm.storeFilters.banner_slug === value.store_banner.key)
        )
      }

      vm.preciseLocation = false
      vm.locationLookupInProgress = false

      // Additional variables
      let allStores = []
      let location = null
      let map = {}
      let mapAlreadyLoaded = false
      let markers = {}
      let onlyShowPickup = true

      //* ****
      // Internal Functions
      //* ****

      // Fetch stores from the storesService
      const initStores = () => {
        const params = {} // Empty param list by default

        // we'll want to fetch a different store list from the API depending on our
        // shop context.
        if (vm.shopContextIntent !== SHOPPING_MODES.INSTORE_LIST) {
          params.show_ecommerce = true
        }

        // Fetch only catering stores
        if (vm.shopContextIntent === SHOPPING_MODES.CATERING) {
          params.show_catering = true
        } else if (vm.shopContextIntent === SHOPPING_MODES.PICKUP && onlyShowPickup === true) {
          params.show_pickup = true
        }

        return storesService.fetch(params).then(() => {
          allStores = storesService.stores
        })
      }

      // Function to drive selecting a store when selecting a map marker
      const selectStoreMarker = store => {
        vm.selectStoreList(store)

        // Index of store selected
        const storeIndex = vm.stores.indexOf(store)

        // Simple function to move stores in the list
        const moveStore = (origin, destination) => {
          const storeToMove = vm.stores[destination]
          vm.stores[destination] = vm.stores[origin]
          vm.stores[origin] = storeToMove
        }

        // Move store to top of list
        moveStore(storeIndex, 0)

        // Scroll stores list to selected store on mobile
        if ($scope.mobile.isMobile) {
          const storesList = document.getElementsByClassName('stores stores-scroll-flag')[0]
          storesList.scrollTop = 0
        }
      }

      // Build our array of map markers
      const drawMarkers = () => {
        allStores.forEach(store => {
          const storeLatitude = store.location.latitude
          const storeLongitude = store.location.longitude

          if (storeLatitude && storeLongitude) {
            const el = document.createElement('div')

            // Set up custom marker styles / event listeners
            el.className = 'marker'
            el.style.backgroundImage = store.show_pickup
              ? `url('${mapHasPickup}')`
              : `url('${mapPinGrey}')`
            el.style.height = '40px'
            el.style.width = '30px'
            el.addEventListener(
              'click',
              (() => {
                return () => {
                  el.style.backgroundImage = `url('${mapPinActive}')`
                  map.flyTo({ center: [storeLongitude, storeLatitude] })
                  selectStoreMarker(store)
                }
              })(store)
            )

            if (window.mapboxgl) {
              const marker = new window.mapboxgl.Marker(el) //eslint-disable-line
                .setLngLat([storeLongitude, storeLatitude])
                .addTo(map)

              markers[store.id] = marker
            }
          }
        })
      }

      const GEO_ERRORS = {
        CURRENT_POSITION: 'CURRENT_POSITION',
        NOT_SUPPORTED: 'NOT_SUPPORTED',
      }

      // Prompt user for location information
      const geolocate = () => {
        let geolocationFetched = false

        // Use their last shopped store if config enabled
        if (config.useLastStoreForGeolocation && auth.user && auth.user.store) {
          location = auth.user.store.location
          vm.search()
        } else if (navigator.geolocation) {
          getCurrentPosition()
            .then(position => {
              geolocationFetched = true
              location = position.coords
              vm.preciseLocation = true
              updateStoresAndUI()
            })
            .catch(e => {
              if (e?.code !== GEO_ERRORS.CURRENT_POSITION) {
                return Promise.reject(e)
              }
              geolocationFetched = true
              updateStoresAndUI()
            })
          // Handle users taking too long to allow geolocation
          $timeout(() => {
            if (!geolocationFetched) {
              vm.search()
            }
          }, 3000)
        } else {
          // No geolocation support
          vm.search()
        }
      }

      function updateStoresAndUI() {
        vm.search()
        $scope.$apply()
      }

      function getCurrentPosition() {
        return new Promise((resolve, reject) => {
          if (navigator.geolocation) {
            // Browser geolocation exists & user accepted
            navigator.geolocation.getCurrentPosition(
              position => {
                resolve(position)
              },
              () => {
                const e = new Error('failed to get current position')
                e.code = GEO_ERRORS.CURRENT_POSITION
                reject(e)
              },
              {
                enableHighAccuracy: true,
              }
            )
          } else {
            const e = new Error('geolocation not supported')
            e.code = GEO_ERRORS.NOT_SUPPORTED
            reject(e)
          }
        })
      }

      vm.useMyLocation = () => {
        if (vm.locationLookupInProgress) {
          return
        }
        vm.terms = ''
        const timerID = setTimeout(() => {
          vm.locationLookupInProgress = true
          $scope.$apply()
        }, 500)
        getCurrentPosition()
          .then(position => {
            location = position.coords
            updateUIAfterUseMyLocation(timerID, true)
            updateStoresAndUI()
          })
          .catch(e => {
            updateUIAfterUseMyLocation(timerID, false)
            switch (e?.code) {
              case GEO_ERRORS.CURRENT_POSITION:
                errorModalService.open(
                  'To help us find your closest store, please go to Settings and share Location access',
                  config.lang.close,
                  null,
                  'Grant Access'
                )
                break
              default:
                return Promise.reject(e)
            }
          })
      }
      function updateUIAfterUseMyLocation(timerID, preciseLocation) {
        clearTimeout(timerID)
        vm.locationLookupInProgress = false
        vm.preciseLocation = preciseLocation
        $scope.$apply()
      }

      // Instantiate the map in the storeSelectorMap container
      const initMap = () => {
        let initialLatitude
        let initialLongitude

        if (auth.user && auth.user.store) {
          initialLatitude = auth.user.store.location.latitude
          initialLongitude = auth.user.store.location.longitude
        } else {
          initialLatitude = allStores[0].location.latitude
          initialLongitude = allStores[0].location.longitude
        }

        if (window.mapboxgl) {
          window.mapboxgl.accessToken = config.mapboxglAccessToken
          map = new window.mapboxgl.Map({
            container: 'storeSelectorMap',
            style: 'mapbox://styles/mapbox/streets-v9',
            center: [initialLongitude, initialLatitude],
            zoom: 7,
          })
          map.addControl(new window.mapboxgl.NavigationControl())
        }

        // Hacky workaround to give map time to initialize
        $timeout(() => {
          map.resize()
          drawMarkers()
          geolocate()
        }, 100)
      }

      const centerOnStore = store => {
        if (!store || !vm.showMap || !map) {
          return
        }

        map.flyTo({
          center: [store.location.longitude, store.location.latitude],
          zoom: 7,
        })
      }

      // Clear out / reset any map markers
      const removeAllMarkers = () => {
        Object.values(markers).forEach(marker => {
          marker.remove()
        })

        markers = {} // Empty markers object (keys are the store id)
      }

      // Initialize the stores & mapbox map
      const initMapboxMap = () => {
        initStores().then(() => {
          // Open map on initialization if client does not have hideStoreSelectorMapOnInit enabled.
          if (!config.hideStoreSelectorMapOnInit && !mapAlreadyLoaded) {
            removeAllMarkers()
            initMap()
            mapAlreadyLoaded = true
          } else {
            geolocate()
          }
        })
      }

      //* ****
      // View Model Functions
      //* ****

      // Toggle the map (only called if the corresponding config is enabled)
      vm.toggleMap = () => {
        vm.showMap = !vm.showMap

        if (vm.showMap && !mapAlreadyLoaded) {
          mapAlreadyLoaded = true
          removeAllMarkers()
          initMap()
        }
      }

      // Select a given store in the list
      vm.selectStoreList = (store, index) => {
        if (vm.currentStore && store && vm.currentStore.id === store.id) {
          vm.currentStore = null
        } else {
          vm.currentStore = store
        }

        // Prevent non-pickup store from being updated for pickup context.
        if (vm.shopContextIntent !== SHOPPING_MODES.PICKUP || store.show_pickup) {
          vm.updateHomeStore(store, index)
        }
      }

      // Filter the markers when toggling pickup / non-pickup stores
      vm.filterMarkers = function () {
        vm.stores = null
        onlyShowPickup = !onlyShowPickup
        return initStores(true).then(() => {
          if (vm.showMap) {
            // If the map is open, redraw markers
            removeAllMarkers()
            drawMarkers()
          }
          geolocate()
        })
      }

      // Provide location information for a given address / general location search term (pretty good case for tossing this into a service)
      vm.geocodeAddress = address => {
        const deferred = $q.defer()
        $http
          .get(
            `https://api.mapbox.com/geocoding/v5/mapbox.places/${address}.json?country=us,ca&access_token=${config.mapboxglAccessToken}`
          )
          .then(response => {
            let location
            if (response.data.features.length > 0) {
              location = {
                latitude: response.data.features[0].center[1],
                longitude: response.data.features[0].center[0],
              }
              if (vm.showMap) {
                if (response.data.features[0].bbox) {
                  // If the map is open & we have a bounding box, do a fancy pan/zoom
                  // eslint-disable-next-line no-unused-expressions
                  map.fitBounds
                    ? map.fitBounds(response.data.features[0].bbox, { maxZoom: 7 })
                    : null
                } else {
                  map.flyTo({ center: response.data.features[0].center }) // Otherwise just jump to the point
                }
              }
            }
            deferred.resolve(location)
          })

        return deferred.promise
      }

      // Search for stores with the given location or search term (via geocode)
      vm.search = () => {
        vm.currentStore = null
        delete vm.stores

        const termsEl = document.querySelector('.store-selector [ng-model="terms"]')
        if (termsEl) {
          document.querySelector('.store-selector [ng-model="terms"]').blur()
        }

        const types = storesService.MATCHED_STORE_TYPES
        const sharedProps = {
          allStores,
          searchTerm: vm.terms,
          intent: shoppingContextService.selectedFulfillmentType,
          storeID: auth.user?.store.id,
        }
        if (vm.terms && location) {
          vm.geocodeAddress(vm.terms).then(location => {
            const { stores, matchedStore } = storesService.filterStores({
              location,
              returnMatchedFor: types.SEARCH_BASED,
              ...sharedProps,
            })
            vm.stores = stores
            centerOnStore(matchedStore)
          })
        } else if (location) {
          const { stores, matchedStore } = storesService.filterStores({
            location,
            returnMatchedFor: vm.preciseLocation ? types.LOCATION_BASED : types.DEFAULT,
            ...sharedProps,
          })
          vm.stores = stores
          centerOnStore(matchedStore)
        } else {
          const { stores, matchedStore } = storesService.filterStores(sharedProps)
          vm.stores = stores
          centerOnStore(matchedStore)
        }
      }

      // Update the home store in the parent controller
      vm.updateHomeStore = (store, index) => {
        vm.loadingIndex = index

        $scope.$parent.updateHomeStore(store).finally(() => {
          delete vm.loadingIndex
        })
      }

      // Set parent shopping selector stage if a user wants to change shopping mode for an unavailable store
      vm.shopAnotherWay = () => {
        auth.setContext({ intent: null }).then(() => {
          $scope.$parent.setShoppingSelectorStage('shoppingContext')
        })
      }

      vm.updateBannerFilter = bannerSlug => {
        vm.storeFilters.banner_slug = bannerSlug
      }

      initMapboxMap()
    }
  )
