import angular from 'angular'
import {
  ANALYTICS,
  CHECKOUT_STEPS,
  EVENTS,
  SOURCES,
} from '@app/angular/services/modules/analytics/analyticsConstants'

/*
 * To understand how checkout events work, please read the instructions here: _Analytics.service.md
 * The constants exist here: analyticsConstants.js
 */

export const REFRESH_MS = 500
export const CHECKOUT_UUID_STORE = 'checkout_UUID'

export default angular
  .module('checkoutAnalyticsModule', [])
  .service(
    'checkoutAnalytics',
    function (
      $location,
      $state,
      $timeout,
      analytics,
      analyticsHelper,
      auth,
      cartService,
      config,
      localStorageService,
      reservationService,
      utilService
    ) {
      const service = this

      Object.assign(service, {
        $state,
        $timeout,
        analytics,
        analyticsHelper,
        config,
        localStorageService,
        utilService,
        checkoutUUID: null,
        checkoutEntered: 0,
        information: {
          $location,
          auth,
          cartService,
          reservationService,
        },
      })

      /**
       * Get default reused information as an object - that way no need to require dependencies all over the project
       * WARNING: !!! Only include easy to access information in here !!!
       *                 Ideally this should have a light footprint
       *
       * @return {object}
       */
      service.DEFAULTS = function () {
        const { $location, auth, cartService, reservationService } = service.information

        return {
          [SOURCES.ANALYTICS_SERVICE]: service.analytics,
          [SOURCES.CART]: cartService.cart || null,
          [SOURCES.USER]: auth.user || null,
          [SOURCES.TIMESLOT]: reservationService.reservation?.timeslot || null,
          [SOURCES.CHECKOUT_ID]: service.checkoutUUID,
          [SOURCES.URL_SLUG]: $location.path() || null,
        }
      }

      /**
       * Private: Helper method to get analytic payloads (default sources and "success" requirement both auto added)
       *
       * @param {object} source - Object of data to be used to generate analytic payloads
       * @param {array} requiredInfo - Array of constants defined above
       *
       * @returns {object|null}
       */
      service._createPayload = function (source = {}, requiredInfo = []) {
        return service.analyticsHelper.createPayload(
          { ...service.DEFAULTS(), ...source },
          requiredInfo
        )
      }

      /**
       * Track the entering/exiting of the checkout flow (including cart) - The tracking survives refreshes
       * @param {boolean} isEnter - true if entering, false if exiting
       */
      service.isInNewCheckoutFlow = analyticsHelper.persistentStateFactory(
        CHECKOUT_UUID_STORE,
        () => service.checkoutUUID,
        newValue => (service.checkoutUUID = newValue)
      )

      // --- CART ANALYTIC METHODS --- //

      /**
       * Triggers analytics for the cart review page
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object} analytics     - The analytics service
       *                             * {object} cart          - The user cart object
       *                             * {string} checkoutUUID  - The UUID representing this checkout session
       *                             * {object} timeslot      - Reservation timeslot
       *                             * {string} urlSlug       - $location.path()
       *                             * {object} user          - Current user
       * @returns {undefined}
       */
      service.cartView = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CART.INFO_ANALYTICS,
          ANALYTICS.CART.INTENT,
          ANALYTICS.CART.ITEM_DETAILS,
          ANALYTICS.FLOW.INSTANCE,
          ANALYTICS.FLOW.SOURCE_TYPE,
          ANALYTICS.TIMESLOT.TIMESLOT_DETAILS,
        ])
        service.analytics.logCheckoutStep(
          CHECKOUT_STEPS[EVENTS.CART.VIEW],
          null,
          service.DEFAULTS().cart.items
        )
        service.analyticsHelper.logAnalytics(EVENTS.CART.VIEW, analyticsPayload, {
          display_name: 'Review Cart',
          success: true,
        })
      }

      /**
       * Triggers analytics for editing cart items quantity
       *
       * @param {object} sources - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                           * {object}  analytics    - The analytics service
       *                           * {object}  cart         - user cart
       *                           * {string}  checkoutUUID - The UUID representing this checkout session
       *                           * {boolean} success       - has edited item quantity with success
       * @returns {undefined}
       */
      service.cartEditQuantity = function (sources) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CART.INFO_ANALYTICS,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CART.EDIT_QUANTITY, analyticsPayload)
      }

      /**
       * Triggers analytics for adding promo code
       *
       * @param {object} sources - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                           * {object}  cart          - The user cart object
       *                           * {string}  checkoutUUID  - The UUID representing this checkout session
       *                           * {object}  offer          - The current offers for the cart
       *                           * {string}  promoCode      - The promo code entered
       *                           * {boolean} success        - Whether it was added or not
       * @returns {undefined}
       */
      service.addPromo = function (sources) {
        const analyticsPayload = service._createPayload(
          // Offers is optional, so set to null to avoid createPayload validation
          { offer: null, ...sources },
          [ANALYTICS.CART.CART_ID, ANALYTICS.FLOW.INSTANCE, ANALYTICS.OFFER.OFFER_DETAILS]
        )

        service.analyticsHelper.logAnalytics(EVENTS.CART.ADD_PROMO, analyticsPayload, {
          promo_code: sources.promoCode.promo_code || sources.promoCode,
        })
      }

      /**
       * Triggers analytics for cart notes editing (auto included: checkoutAnalytics.DEFAULTS)
       *
       * @param {object} sources - An object with the following information:
       *                           * {object}  cart          - The user cart object
       *                           * {string}  checkoutUUID  - The UUID representing this checkout session
       *                           * {string}  note           - The note
       *                           * {boolean} success        - Succesfully edited card notes
       *
       * @returns {undefined}
       */
      service.modifyNote = function (sources) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CART.MODIFY_NOTE, analyticsPayload, {
          note: sources.note,
        })
      }

      /**
       * Triggers analytics for the cart review checkout click button
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object} cart          - The user cart object
       *                             * {string} checkoutUUID  - The UUID representing this checkout session
       *                             * {object} timeslot      - Reservation timeslot
       *                             * {object} user          - Current user
       * @returns {undefined}
       */
      service.checkoutClick = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CART.INTENT,
          ANALYTICS.FLOW.INSTANCE,
          ANALYTICS.TIMESLOT.TIMESLOT_DETAILS,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CART.CHECKOUT_CLICK, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for entering the checkout pages
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart          - The user cart object
       *                             * {string}  checkoutUUID  - The UUID representing this checkout session
       *                             * {object}  timeslot      - Reservation timeslot
       *                             * {object}  user          - Current user
       * @returns {undefined}
       */
      service.enterCheckoutFlow = function (sources = {}) {
        if (typeof service.isInNewCheckoutFlow === 'function') {
          service.isInNewCheckoutFlow(true)
        }

        if (!service.checkoutEntered) {
          const analyticsPayload = service._createPayload(sources, [
            ANALYTICS.CART.CART_ID,
            ANALYTICS.CART.INTENT,
            ANALYTICS.FLOW.INSTANCE,
            ANALYTICS.TIMESLOT.TIMESLOT_DETAILS,
          ])
          service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.ENTER_CHECKOUT, analyticsPayload, {
            success: true,
          })
        }

        service.checkoutEntered = 1
      }

      /**
       * Triggers analytics for exiting the checkout pages
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart          - The user cart object
       *                             * {string}  checkoutUUID  - The UUID representing this checkout session
       *                             * {string}  urlSlug       - $location.path()
       * @returns {undefined}
       */
      service.exitCheckoutFlow = function (sources = {}, sendImmediately = false) {
        if (sendImmediately) {
          service._logExitCheckoutEvent(sources)
        } else if (service.checkoutEntered) {
          // eslint-disable-next-line no-plusplus
          service.checkoutEntered++

          service.$timeout(() => {
            if (service.checkoutEntered > 1) {
              service._logExitCheckoutEvent(sources)
              service.checkoutEntered = 0
            }
          }, REFRESH_MS)
        }
      }

      service._logExitCheckoutEvent = function (sources) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
          ANALYTICS.FLOW.SOURCE_TYPE,
          ANALYTICS.FLOW.SOURCE_VALUE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.EXIT_CHECKOUT, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for entering upsell
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  analytics       - The analytics service
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  timeslot        - Reservation timeslot
       *                             * {object}  upsellProducts  - The upsell product source
       * @returns {undefined}
       */
      service.enterCheckoutUpsell = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CART.ITEM_DETAILS,
          ANALYTICS.CHECKOUT.UPSELL_ITEMS,
          ANALYTICS.FLOW.INSTANCE,
          ANALYTICS.TIMESLOT.TIMESLOT_DETAILS,
        ])
        service.analytics.logCheckoutStep(
          CHECKOUT_STEPS[EVENTS.CHECKOUT.ENTER_UPSELL],
          null,
          service.DEFAULTS().cart.items
        )
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.ENTER_UPSELL, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for exiting upsell
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  analytics       - The analytics service
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  timeslot        - Reservation timeslot
       *                             * {object}  upsellProducts  - The upsell product source
       * @returns {undefined}
       */

      service.exitCheckoutUpsell = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CART.ITEM_DETAILS,
          ANALYTICS.CHECKOUT.UPSELL_ITEMS,
          ANALYTICS.FLOW.INSTANCE,
          ANALYTICS.TIMESLOT.TIMESLOT_DETAILS,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.COMPLETE_UPSELL, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for entering the review of checkout page (new checkout flow)
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       * @returns {undefined}
       */
      service.enterCheckoutReview = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analytics.logCheckoutStep(
          CHECKOUT_STEPS[EVENTS.CHECKOUT.ENTER_REVIEW],
          null,
          service.DEFAULTS().cart.items
        )
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.ENTER_REVIEW, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for clicking edit cart
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       * @returns {undefined}
       */
      service.clickEditCart = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.CLICK_EDIT_CART, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for viewing fulfillment options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object} cart             - The user cart object
       *                             * {string} checkoutUUID     - The UUID representing this checkout session
       *                             * {object} user             - Current user
       * @returns {undefined}
       */
      service.viewFulfillmentOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CART.INTENT,
          ANALYTICS.CHECKOUT.CONTEXT,
          ANALYTICS.CHECKOUT.SHOP,
          ANALYTICS.FLOW.INSTANCE,
        ])

        // This is needed as this was a step in the old checkout flow and in the new checkout it is an option
        service.config.newCheckoutFlow.checkoutPage.enabled
          ? service.analytics.logCheckoutOption(
              CHECKOUT_STEPS[EVENTS.CHECKOUT.ENTER_REVIEW],
              EVENTS.CHECKOUT.VIEW_FULFILLMENT
            )
          : service.analytics.logCheckoutStep(
              CHECKOUT_STEPS[EVENTS.CHECKOUT.VIEW_FULFILLMENT],
              null,
              service.DEFAULTS().cart.items
            )

        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.VIEW_FULFILLMENT, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for saving fulfillment options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  user            - Current user
       *                             * {boolean} success          - Whether the update was successful or not
       * @returns {undefined}
       */
      service.editFulfillmentOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CART.INTENT,
          ANALYTICS.CHECKOUT.CONTEXT,
          ANALYTICS.CHECKOUT.SHOP,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.EDIT_FULFILLMENT, analyticsPayload)
      }

      /**
       * Triggers analytics for viewing confirm fulfillment options page in the old checkout flow
       *
       * TODO: this can be removed if the old checkout flow becomes obsolete
       */
      service.confirmFulfillmentOptions = function () {
        service.analytics.logCheckoutStep(
          CHECKOUT_STEPS[EVENTS.CHECKOUT.CONFIRM_FULFILLMENT],
          null,
          service.DEFAULTS().cart.items
        )
      }

      /**
       * Triggers analytics for viewing timeslot options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  timeslot        - Reservation timeslot
       *                             * {boolean} success
       * @returns {undefined}
       */
      service.viewTimeslotOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
          ANALYTICS.TIMESLOT.TIMESLOT_DETAILS,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.VIEW_TIMESLOTS, analyticsPayload)
      }

      /**
       * Triggers analytics for saving timeslot options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {boolean} success          - Timeslot succesfully saved or not
       *                             * {object}  timeslot        - Reservation timeslot
       * @returns {undefined}
       */
      service.editTimeslotOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
          ANALYTICS.TIMESLOT.TIMESLOT_DETAILS,
          ANALYTICS.RESERVATION.FULFILLMENT_DATE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.SELECT_TIMESLOT, analyticsPayload)
      }

      /**
       * Triggers analytics for viewing contact options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  contact          - The currently selected contact info
       * @returns {undefined}
       */
      service.viewContactOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.VIEW_CONTACT, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for saving contact options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  contact          - The currently selected contact info
       *                             * {boolean} success          - Whether the update was successful or not
       * @returns {undefined}
       */
      service.editContactOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.EDIT_CONTACT, analyticsPayload)
      }

      /**
       * Triggers analytics for editing payment options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  payment          - The currently payment info
       *                             * {string}  user_action      - The way in which a payment was added
       *
       * @returns {undefined}
       */
      service.editPaymentOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CHECKOUT.PAYMENT,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.EDIT_PAYMENT, analyticsPayload, {
          paymentLabel: sources.paymentLabel,
          success: true,
          user_action: sources.user_action,
        })
      }

      /**
       * Triggers analytics for the add new card iframe load
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       * @returns {undefined}
       */
      service.addCreditCardOpen = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
        ])

        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.ADD_CREDIT_CARD_OPEN, analyticsPayload)
      }

      /**
       * Triggers analytics for adding a new card or a new gift card
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  payment          - The current payment info
       *                             * {object}  success          - Whether added successfully
       *                             * {string}  user_action      - The way in which a payment was added
       * @returns {undefined}
       *
       * TODO There is no global hook for monitoring adding cards - this needs to be added into each provider
       * TODO Wull be done as a seperate task since it touches every payment provider
       */
      service.addPaymentOption = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CHECKOUT.PAYMENT,
          ANALYTICS.FLOW.INSTANCE,
        ])

        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.ADD_PAYMENT, analyticsPayload, {
          user_action: sources.user_action,
        })
      }

      /**
       * Triggers analytics for viewing payment options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  contact          - The currently selected contact info
       * @returns {undefined}
       */
      service.viewPaymentOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CHECKOUT.PAYMENT,
          ANALYTICS.FLOW.INSTANCE,
        ])
        // This is needed as this was a step in the old checkout flow and in the new checkout it is an option
        service.config.newCheckoutFlow.checkoutPage.enabled
          ? service.analytics.logCheckoutOption(
              CHECKOUT_STEPS[EVENTS.CHECKOUT.ENTER_REVIEW],
              EVENTS.CHECKOUT.VIEW_PAYMENT
            )
          : service.analytics.logCheckoutStep(
              CHECKOUT_STEPS[EVENTS.CHECKOUT.VIEW_PAYMENT],
              null,
              service.DEFAULTS().cart.items
            )

        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.VIEW_PAYMENT, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for viewing tip options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {object}  tip              - The currently tip info
       * @returns {undefined}
       */
      service.viewTipOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.VIEW_TIP, analyticsPayload, {
          success: true,
          tip: sources.tip,
        })
      }

      /**
       * Triggers analytics for saving tip options
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  cart            - The user cart object
       *                             * {string}  checkoutUUID    - The UUID representing this checkout session
       *                             * {boolean} success          - Whether the update was successful or not
       *                             * {object}  tip              - The currently tip info
       * @returns {undefined}
       */
      service.editTipOptions = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CHECKOUT.TIP,
          ANALYTICS.FLOW.INSTANCE,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.EDIT_TIP, analyticsPayload)
      }

      /**
       * Triggers analytics for clicking place order
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object} analytics     - The analytics service
       *                             * {object} cart          - The user cart object
       *                             * {string} checkoutUUID  - The UUID representing this checkout session
       *                             * {object} order          - The user order object
       *                             * {object} timeslot      - Reservation timeslot
       *                             * {string} urlSlug       - $location.path()
       *                             * {object} user          - Current user
       * @returns {undefined}
       */
      service.placeOrderClick = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CART.INFO_ANALYTICS,
          ANALYTICS.CART.INTENT,
          ANALYTICS.CART.ITEM_DETAILS,
          ANALYTICS.CHECKOUT.ORDER,
          ANALYTICS.FLOW.INSTANCE,
          ANALYTICS.TIMESLOT.TIMESLOT_DETAILS,
        ])
        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.CLICK_PLACE_ORDER, analyticsPayload, {
          success: true,
        })
      }

      /**
       * Triggers analytics for checkout completion
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                           * {object} analytics     - The analytics service
       *                           * {string} checkoutUUID  - The UUID representing this checkout session
       *                           * {object} cart          - The user cart object
       *                           * {object} order          - The user order object
       *                           * {object} timeslot      - Reservation timeslot
       *                           * {string} urlSlug       - $location.path()
       *                           * {object} user          - Current user
       * @returns {undefined}
       */
      service.orderComplete = function (sources = {}) {
        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.CART.INFO_ANALYTICS,
          ANALYTICS.CART.INTENT,
          ANALYTICS.CART.ITEM_DETAILS,
          ANALYTICS.CHECKOUT.ORDER,
          ANALYTICS.FLOW.INSTANCE,
          ANALYTICS.TIMESLOT.TIMESLOT_DETAILS,
        ])

        service.analyticsHelper.logAnalytics(EVENTS.CHECKOUT.PLACE_ORDER_COMPLETE, analyticsPayload)
      }
    }
  )
