import angular from 'angular'

import template from './loyaltyInfo.html'

export default angular
  .module('loyaltyInfoServiceModule', [])
  .service('loyaltyInfoService', function registrationService($uibModal) {
    const service = this

    service.open = function () {
      const modal = $uibModal.open({
        template,
        controller: 'LoyaltyInfoController',
        keyboard: true,
        backdrop: true,
      })

      return modal.result
    }
  })
