import angular from 'angular'
import isCordova from '@app/utils/isCordova'
import urlParser from '@app/utils/urlParser'

export default angular
  .module('universalLinksServicesModule', [])

  .service('universalLinksService', function universalLinksService($location, $rootScope) {
    const service = this

    // Application Constructor
    service.initialize = function () {
      service.bindEvents()
    }

    // Bind Event Listeners
    service.bindEvents = function () {
      document.addEventListener('deviceready', service.onDeviceReady, false)
    }

    // deviceready Event Handler
    service.onDeviceReady = function () {
      if (window.universalLinks) {
        window.universalLinks.subscribe('urlDeepLinkCapture', service.didLaunchAppFromLink)
      }
    }

    // Strips the local path from the URL of the event and changes the window's location to it.
    // For a Hash URL, this takes everything after the '#'.
    // For a HTML5 URL, this takes the path & query.
    service.didLaunchAppFromLink = function (eventData) {
      // Opt out and let AppsFlyer SDK handle OneLink
      if (eventData?.host?.endsWith?.('onelink.me')) {
        return
      }

      const urlParts = urlParser(eventData.url)
      let cleanURL

      if (urlParts.hash && (urlParts.hash[1] === '/' || urlParts.hash[1] === '?')) {
        cleanURL = urlParts.hash.slice(1)
      } else {
        cleanURL = urlParts.pathname + urlParts.search
      }

      // Search parameters are sometimes ignored on cordova
      // location.replace('#' + cleanURL);
      $location.url(cleanURL)
      $rootScope.$apply()
    }
  })

  .run(($location, $rootScope, universalLinksService) => {
    if (isCordova()) {
      universalLinksService.initialize()
    }
  })
