import angular from 'angular'

export default angular
  .module('UnataFirstNameLabelModule', [])

  .component('unataFirstNameLabel', {
    controller(auth, config, lang) {
      this.auth = auth
      // eslint-disable-next-line eqeqeq
      this.isTrialUser = () => auth.user.status == 'trial'
      this.getUserName = () => auth.user.first_name
      this.getFirstNameText = () => {
        return lang.template(config.lang.registrationDropdownFirstNameText, {
          firstName: auth.user.first_name,
        })
      }
    },
    template:
      '<span ng-if="!$ctrl.isTrialUser() && $ctrl.auth.user.first_name" aria-label="Logged in as {{$ctrl.getUserName()}}">{{$ctrl.getFirstNameText()}}</span>',
  })
