import angular from 'angular'
import isMobile from 'ismobilejs'
import isMobileDevice from '@app/utils/isMobile'
import isMobileApp from '@app/utils/isMobileApp'
import { getUrbanAirshipChannelId } from '@app/utils/getUrbanAirshipChannelId'
import { getCodePushBundleLabel } from '@app/utils/codePushUtils'

export const DEVICE_TYPES = {
  IOS: 'ios',
  ANDROID: 'android',
  UNKNOWN: 'unknown',
}

export default angular
  .module('mobileServicesModule', [])
  .service(
    'mobileService',
    function mobileService($document, $rootScope, $timeout, $window, config) {
      const service = this

      service.isMobileApp = isMobileApp

      service.getMobileOs = function () {
        if (isMobile.apple.device) return DEVICE_TYPES.IOS

        if (isMobile.android.device) return DEVICE_TYPES.ANDROID
      }

      service.isIOS = () => isMobile.apple.device

      service.iosWebViewCheck = () => {
        try {
          const { messageHandlers } = window.webkit || {}
          return (
            messageHandlers && service.isMobileApp() && service.getMobileOs() === DEVICE_TYPES.IOS
          )
        } catch (err) {
          return false
        }
      }

      const setIsMobile = function () {
        service.isMobile = service.isMobileApp() || isMobileDevice(config)

        if (service.isMobile) {
          window.isMobile = true
          angular.element($document[0].body).addClass('mobile')
          angular.element($document[0].body).removeClass('desktop')
        } else {
          window.isMobile = false
          angular.element($document[0].body).addClass('desktop')
          angular.element($document[0].body).removeClass('mobile')

          // When on a tablet with mobile web disabled, enable pinch zooming

          if (isMobile.tablet && !config.mobileWeb.tablet) {
            allowPinchZooming()
          }
        }

        /*
      Allows specific targeting of native mobile application styles.
      The not-native class avoids use of :not(.native) selector which is not IE8 supported
       */
        if (service.isMobileApp()) {
          angular.element($document[0].body).addClass('native')
          angular.element($document[0].body).removeClass('not-native')
        } else {
          angular.element($document[0].body).addClass('not-native')
          angular.element($document[0].body).removeClass('native')
        }
      }

      // Remove the mobile viewport setting to allow pinch zooming.

      function allowPinchZooming() {
        angular.element(document.querySelector('#viewport-meta'))[0].setAttribute('content', '')
      }

      setIsMobile()
      angular.element($window).bind('resize', () => {
        setIsMobile()
        $rootScope.$apply()
      })

      document.addEventListener('deviceready', () => {
        // Hide the splash screen

        if (navigator.splashscreen) {
          navigator.splashscreen.hide()
        }

        // Expose the mobile app version using cordova-plugin-app-version
        if (service.isMobileApp()) {
          if (window.cordova.getAppVersion.getVersionNumber) {
            window.cordova.getAppVersion.getVersionNumber(version => {
              service.appVersion = version
            })
          }
          if (window.cordova.getAppVersion.getVersionCode) {
            window.cordova.getAppVersion.getVersionCode(version => {
              service.buildNumber = version
            })
          }
          if (window.cordova.getAppVersion.getAppName) {
            window.cordova.getAppVersion.getAppName(name => {
              service.appName = name
            })
          }
          if (config.urbanAirshipEnabled) {
            getUrbanAirshipChannelId(id => (service.UrbanAirshipChannelId = id))
          }
          getCodePushBundleLabel(label => (service.codePushBundleLabel = label))
        }
      })
    }
  )
