import angular from 'angular'
import { isReactRoute } from '@app/react/routes'
import { HTTP_ERRORS } from '../../constants/httpErrors'

export default angular
  .module('ErrorPage', [])
  .config($stateProvider => {
    const ERROR_CODES = [
      HTTP_ERRORS.NOT_FOUND,
      HTTP_ERRORS.FORBIDDEN,
      HTTP_ERRORS.SERVICE_UNAVAILABLE,
    ]
    ERROR_CODES.forEach(val => {
      $stateProvider.state(`main.${val}`, {
        url: `/${val}`,
        views: {
          'content@': {
            template: require('./errorPage.html'),
            controller($location, $state, config, lang, mobileService, utilService) {
              // TODO: Remove this logic after CUS cutover is complete
              if (config.env.shortCode === 'cus') {
                const cus_pbi_endpoints_regex = /^\/store\/(orders.*|account.*|items.*|costco.*)/
                if (cus_pbi_endpoints_regex.test($location.$$url)) {
                  $state.go('main.home')
                }
              }

              this.image = mobileService.isMobile
                ? ''
                : utilService.loadAssetImage(`images/${val}.png`)
              this.title = lang.values[`http${val}Title`]
              this.message = lang.values[`http${val}Message`]
              // $state.href will prepend the `#` if html5 mode is not enabled.
              this.buttonUrl =
                config.errorPages.buttonHref || $state.href(...config.errorPages.buttonSref)
              this.buttonText = config.errorPages.buttonText

              // Render an empty view when on a react route because we don't want an error page rendered in angular when the app is on a valid react route. Also avoids seeing the error page when navigating from react to angular route.
              this.isReactRoute = isReactRoute($location.path())
            },
            controllerAs: '$ctrl',
          },
        },
      })
    })
  })

  // TODO: Create and use a React Component.
  .component('errorPageComponent', {
    bindings: {
      buttonText: '<',
      buttonUrl: '<',
      image: '<',
      message: '<',
      title: '<',
    },
    template: `
      <img ng-if="::$ctrl.image" ng-src="{{::$ctrl.image}}" alt="{{::$ctrl.title}}" />
      <h1>{{::$ctrl.title}}</h1>
      <p>{{::$ctrl.message}}</p>
      <div class="unata-button-container" >
        <a class="button" ng-href="{{::$ctrl.buttonUrl}}" >{{::$ctrl.buttonText}}</a>
      </div>
    `,
  })

  .run(($rootScope, $state, analytics) => {
    $rootScope.$on(
      '$stateChangeError',
      (event, toState, toParams, fromState, fromParams, error = {}) => {
        const errorData = error.data || error
        if (error.status === HTTP_ERRORS.NOT_FOUND) {
          analytics.logEvent('not found', 'backend error', '404')
          $state.go('main.404')
        }
        // Service Unavailable, do not change the location in the url so refreshing is easy.
        else if (error.status === HTTP_ERRORS.SERVICE_UNAVAILABLE) {
          analytics.logEvent('not found', 'backend error', '503')
          $state.go('main.503', {}, { location: false })
        }
        // Do not intercept the PII errors, do not change the location in the url so refreshing is easy.
        else if (
          error.status === HTTP_ERRORS.FORBIDDEN &&
          errorData.error !== HTTP_ERRORS.PII_UNAUTHORIZED
        ) {
          analytics.logEvent('not found', 'backend error', '403')
          $state.go('main.403', {}, { location: false })
        }
      }
    )
  })
