import angular from 'angular'

export default angular
  .module('FlyerPreviewModalControllerModule', [])
  .controller(
    'FlyerPreviewModalController',
    function FlyerPreviewModalController(
      $state,
      $uibModalInstance,
      currentFlyer,
      flyerCategoriesAndPdfsService,
      flyerPreviewService,
      lang,
      mobileService,
      previewFlyers,
      previewToken
    ) {
      this.isMobile = mobileService.isMobile
      this.lang = lang

      this.currentFlyer = currentFlyer
      this.previewFlyers = previewFlyers

      // Initialize flyer radio to show the current flyer being viewed as selected
      this.selectedPreviewToken = previewToken || 'current'

      this.goToFlyer = toFlyerPreviewToken => {
        toFlyerPreviewToken = toFlyerPreviewToken === 'current' ? null : toFlyerPreviewToken
        const currentFlyerPreviewToken = flyerPreviewService.getPreviewToken()

        // eslint-disable-next-line eqeqeq
        if (toFlyerPreviewToken == currentFlyerPreviewToken) {
          // Dismiss modal if they select the flyer currently being viewed
          $uibModalInstance.dismiss()
        } else {
          // Else, set the new previewToken and navigate to the selected flyer
          flyerPreviewService.setPreviewToken(toFlyerPreviewToken)

          // Clear the flyer categories to cause a refetch
          flyerCategoriesAndPdfsService.clearFlyerCategories()
          $state.go('main.shop.flyer', null, { reload: true })
        }
      }
    }
  )
