import dayjs from 'dayjs'
import angular from 'angular'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { DATE_OF_BIRTH_FORMAT } from '@app/angular/constants/alcoholValidation'
import { FULFILLMENT_TYPES, CHECKOUT_STEPS_STATUS } from '@app/angular/constants/checkout'
import alcoholValidationErrorModal from '@app/angular/services/templates/alcoholValidation/alcoholValidationErrorModal.html'

export default angular
  .module('checkoutServiceModule', [])
  .service(
    'checkoutService',
    function (
      $q,
      $rootScope,
      $state,
      addressList,
      alcoholValidation,
      analytics,
      auth,
      cartService,
      checkoutAnalytics,
      config,
      errorModalService,
      lang,
      mobileNativeHrefService,
      promoCodeService,
      registrationService,
      reservationService
    ) {
      Object.assign(this, {
        $q,
        $rootScope,
        auth,
        config,
        cartService,
        error: null,
        addressList,
        contact: null,
        alcoholValidation,
        errorModalService,
        reservationService,
        deliveryAddress: null,
        validateWithFees: true,
        isCartValidated: false,
        lastUsedPaymentInstrument: null,
        orderDeliveryInstructions: null,
        isBillingAddressSameAsDelivery: true,
        fulfillmentType: auth.getContext().intent,
        stepsStatuses: { ...CHECKOUT_STEPS_STATUS },
      })

      /**
       * Setter for the delivery address attribute
       * @param {object} address
       */
      this.setDeliveryAddress = address => {
        this.deliveryAddress = address
      }

      /**
       * Setter for the contact attribute
       * @param {object} contact
       */
      this.setContact = contact => {
        this.contact = contact
      }

      /**
       * Setter for the order delivery instructions attribute
       * @param {boolean} leaveUnattended
       */
      this.setOrderDeliveryInstructions = leaveUnattended => {
        this.orderDeliveryInstructions = { leaveUnattended }
      }

      this.clearOrderDeliveryInstructions = () => {
        this.orderDeliveryInstructions = { leaveUnattended: null }
      }

      /**
       * Validates a given billing address object
       *
       * @param {object} billingAddress
       * @returns {boolean}
       */
      this.isBillingAddressValid = ({ address1, city, province, postal_code }) => {
        if (
          this.fulfillmentType === FULFILLMENT_TYPES.DELIVERY &&
          this.isBillingAddressSameAsDelivery
        ) {
          return true
        }

        return address1 && city && province && postal_code
      }

      /**
       * Returns promise that resolves with the current selected address
       * Addresses are saved in different places depending on if the retailer is
       * using multiple delivery addresses or not
       *
       * @param { boolean } isCheckoutScreen  - set to to true if checking out
       * @returns { Promise }
       */
      this.getSelectedDeliveryAddress = isCheckoutScreen => {
        const deferred = this.$q.defer()
        const currentContext = this.auth.getContext()

        const findAddress = address => address.id === currentContext.address_id

        const existingAddress = cartService.cart?.order?.delivery_address
        if (!isCheckoutScreen && existingAddress) {
          deferred.resolve(existingAddress)
        } else if (this.config.enableMultipleDeliveryAddresses) {
          if (this.addressList.addressesLoaded) {
            deferred.resolve(this.addressList.addressItems.find(findAddress))
          } else {
            this.addressList.fetchAddresses().then(addresses => {
              deferred.resolve(addresses.find(findAddress))
            })
          }
        } else if (this.addressList.addressesLoaded) {
          deferred.resolve(this.addressList.addressItems[0])
        } else {
          this.addressList.fetchAddresses().then(addresses => {
            deferred.resolve(addresses[0])
          })
        }

        return deferred.promise
      }

      this.setError = error => {
        this.error = error ? error.message : null
      }

      /**
       * Updates the cart and fees validation status.
       *
       * When the cart size has changed the next cart validation call should include the with_fees param
       * and the validation status of the cart is set to false
       *
       * @param { boolean } hasChanged
       *
       * @returns { undefined }
       */
      this.handleCartCompareChange = hasChanged => {
        this.validateWithFees = this.validateWithFees || hasChanged

        if (this.isCartValidated && hasChanged) {
          this.isCartValidated = false
        }
      }

      this.setLastUsedPaymentInstrument = paymentInstrument => {
        this.lastUsedPaymentInstrument = paymentInstrument
      }

      this.getLastUsedPaymentInstrument = () => {
        return this.lastUsedPaymentInstrument
      }

      /**
       * @private Gets and modifies order params for checkout
       *
       * @param { object } addressList        - addressList service
       * @param { object } auth               - auth service
       * @param { object } alcoholValidation  - alcoholValidation service
       * @param { object } config             - config service
       * @param { object } paymentObject      - The payment as acquired from the api and updated by the gateway for checkout
       *
       * @returns { Object }
       */
      this._getOrderParams = (addressList, auth, alcoholValidation, config, paymentObject) => {
        const context = auth.getContext()
        const ordersParams = angular.copy(paymentObject)

        if (config.alcoholCompliance.enabled) {
          ordersParams.user_birthday = alcoholValidation.getDateOfBirth()
            ? dayjs(alcoholValidation.getDateOfBirth(), DATE_OF_BIRTH_FORMAT).toISOString()
            : null
        }

        if (context.intent === SHOPPING_MODES.DELIVERY) {
          ordersParams.delivery_address = { ...this.deliveryAddress }
        }

        ordersParams.contact_info = this._getContactInfo()

        if (this.orderDeliveryInstructions) {
          ordersParams.leave_unattended = Boolean(
            // this.cart.order?.leave_unattended is defined only for modify case, otherwise...
            this.cartService.cart.order?.leave_unattended ??
              this.orderDeliveryInstructions.leaveUnattended
          )
        }

        return ordersParams
      }

      /**
       * Returns the contact information for the order payload
       *
       * @returns {object}
       */
      this._getContactInfo = () => {
        const { intent } = this.auth.getContext()
        const contactSource = intent === SHOPPING_MODES.PICKUP ? this.contact : this.deliveryAddress

        if (!contactSource) return

        const { first_name, last_name, phone_number } = contactSource

        /* Avoid inserting contact info if any mandatory fields are missing,
         * because the backend won't validate contact info unless all fields
         * are present.
         */
        if ([first_name, last_name, phone_number].some(k => k == null)) {
          return
        }

        return { first_name, last_name, phone_number }
      }

      /**
       * Performs a checkout
       *
       * Order of operations:
       * - Verify payment is ready with gateway
       * - Set last used payment for use with IC Express (mainly for Stripe)
       * - Update server with payment
       * - Perform checkout order
       * - delete cartService.dateOfBirth via alcoholValidation.clearDateOfBirth()
       * - If vantiv: Remove non saving cards
       *
       * @param { object } paymentObject   - The payment as acquired from the api and updated by the gateway for checkout
       * @param { object } gatewayService  - The gateway service
       *
       * @returns { Promise }
       */
      this.performCheckout = (paymentObject, gatewayService, additionalParams = {}) => {
        const { addressList, auth, alcoholValidation, config } = this
        const {
          checkout,
          checkoutComplete,
          onAfterCheckoutError,
          verifyPayment,
          customErrorHandler,
        } = gatewayService || {}
        const paymentDetails = paymentObject && paymentObject[paymentObject.payment_type]
        const { cart_id, checkoutErrorCallback, postCheckoutRoute } = additionalParams
        let showModalOnDefaultError = false

        paymentObject.cart_id = cart_id

        if (paymentObject.gift_cards) {
          // Making sure that the amount is a string. During order creation, the gift cart amount has the right type
          // but in order modification it is being set to a number
          paymentObject.gift_cards.forEach(
            giftCard => (giftCard.amount = giftCard.amount.toString())
          )
        }

        return Promise.resolve()
          .then(() => verifyPayment && verifyPayment())
          .then(() =>
            this.setLastUsedPaymentInstrument(paymentDetails && paymentDetails.payment_instrument)
          )
          .then(() => paymentObject.put())
          .then(response => {
            showModalOnDefaultError = true
            const orderParams = this._getOrderParams(
              addressList,
              auth,
              alcoholValidation,
              config,
              paymentObject
            )

            return (
              checkout &&
              checkout({ ...orderParams, ...additionalParams }, { response, postCheckoutRoute })
            )
          })
          .then(order => {
            alcoholValidation.clearDateOfBirth()
            promoCodeService.clearSuccessMessage()
            checkoutComplete && checkoutComplete()
            checkoutAnalytics.orderComplete({ order })
            return order
          })
          .then(() => cartService.resetToPreModCart())
          .then(() => cartService.resetToPreModContext())
          .catch(error => {
            this.logCheckoutErrorAnalytics(error)
            handleCheckoutError(error, customErrorHandler, showModalOnDefaultError)
              .then(() => onAfterCheckoutError && onAfterCheckoutError())
              .finally(() => checkoutErrorCallback && checkoutErrorCallback())

            throw error
          })
          .finally(() => {
            this.$rootScope.isAppLoading = false
          })
      }

      this.logCheckoutErrorAnalytics = error => {
        const EVENT = 'error.modal.open'
        const payload = {
          state: $state?.current?.name,
          status: error?.status,
          error: error?.data?.error,
          message: error?.data?.message || error?.message,
          request_id: error?.headers?.()?.['x-event-id'],
        }
        analytics.logSegmentEvent(EVENT, payload)
      }

      /**
       * Returns a payload object containing the required data for a given
       * *fulfillmentType*
       *
       *
       * @param {string} fulfillmentType
       *
       * @returns {object} Object containing the checkout validation payload
       */
      this.getValidationPayload = fulfillmentType => {
        const { store } = this.auth.user
        const payload = {
          timeslot: this.reservationService?.reservation?.timeslot,
        }

        if (this.cartService.getHasAlcohol() && this.config.alcoholCompliance.enabled) {
          payload.user_birthday = dayjs(this.cartService.dateOfBirth, DATE_OF_BIRTH_FORMAT)
            .startOf('day')
            .toISOString()
        }

        if (fulfillmentType === FULFILLMENT_TYPES.PICKUP) {
          const { first_name, last_name, phone_number } = this.contact

          payload.store = store
          payload.contact_info = {
            first_name,
            last_name,
            phone_number,
          }
        } else if (fulfillmentType === FULFILLMENT_TYPES.DELIVERY) {
          payload.delivery_address = this.deliveryAddress
        }

        payload.cart_id = this.cartService.getCartId(this.cartService.getCurrentCartType())

        return payload
      }

      this.orderPaginationStack = []

      /**
       * @private Removes Vantiv cards we do not wish to save
       *
       * @param { Error } error                     - The error as returned by the promise chain
       * @param { boolean } showModalOnDefaultError - true to show a modal on a default generic error
       *
       * @returns { Promise }
       */
      function handleCheckoutError(error, customErrorHandler, showModalOnDefaultError) {
        if (typeof customErrorHandler === 'function') {
          return customErrorHandler(error)
        }

        return defaultErrorHandler(error, showModalOnDefaultError)
      }

      function defaultErrorHandler(error, showModalOnDefaultError) {
        const currentState = $state.current.name
        const { error: err, message } = error?.data || {}

        switch (err) {
          case HTTP_ERRORS.ALCOHOL_FULFILLMENT_ERROR:
            return errorModalService.openCustom(alcoholValidationErrorModal, { lang })
          case HTTP_ERRORS.SESSION_EXPIRED_ERROR:
          case HTTP_ERRORS.PII_UNAUTHORIZED:
          case HTTP_ERRORS.MISSING_CART:
            return errorModalService
              .open(lang.values.expiredTokenErrorMessage, 'OK', null)
              .then(() => {
                registrationService.promptLogin(
                  currentState,
                  '',
                  '',
                  '',
                  lang.values.loginToContinue
                )
              })
          case HTTP_ERRORS.CONNECT_ERROR:
            if (!showModalOnDefaultError) return $q.resolve()
            return errorModalService.open(
              lang.values.connectErrorOverrides[message] || message || error.message,
              lang.values.ok
            )
          default:
            if (!showModalOnDefaultError) return $q.resolve()
            return errorModalService.open(message || error.message, lang.values.ok)
        }
      }
    }
  )
