import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'
import { CART_COMPARE_TRIGGERS } from '@app/angular/constants/context'
import { UNIT_OF_MEASURE } from '@app/angular/constants/uom'

import template from './views/compareCart.component.html'

export default angular.module('CartCompareModule', []).component('cartCompare', {
  template,
  bindings: {
    customCartCompareConfig: '<?',
  },
  controller($q, cartService, compareCartService, lang, offersService, imageURLService) {
    this.lang = lang
    this.imageURLService = imageURLService
    this.UNIT_OF_MEASURE = UNIT_OF_MEASURE

    const categorizedProducts = cartService.getAllStoreProductsInCart()

    const cartCompareProductLists = compareCartService.cartComparelists

    // Destructure and rename cart compare lists

    const {
      configurable_product_leadtime: configurableProductLeadtimeList,
      offers: offersList,
      product_blackout: productBlackoutList,
      product_inventory: productInventoryList,
      product_pricing: productPricingList,
    } = cartCompareProductLists

    const changedProductLists = _.omit(cartCompareProductLists, 'offers')
    const changedProductIds = fp.flow(
      fp.flatMap(items => items),
      fp.filter(item => typeof item === 'object' && item !== null),
      fp.map(item => item.id),
      fp.uniq,
      fp.compact
    )(changedProductLists)

    const _filterProducts = products => {
      let filteredProducts = [...products]
      const { omitChanges = [] } = this.customCartCompareConfig || {}
      const shouldOmitPriceChanges = omitChanges.includes(CART_COMPARE_TRIGGERS.PRICE)

      if (shouldOmitPriceChanges) {
        const pricingChanges = new Set(changedProductLists.product_pricing.map(p => p.id))

        filteredProducts = filteredProducts.filter(product => !pricingChanges.has(product.id))
      }

      return filteredProducts
    }

    const getStoreProductCategory = item => {
      return item.store_product?.categories?.[0]?.name || lang.values.uncategorizedCategory
    }

    // this.flaggedProductsByCategory is an object of the user's cart products that cart compare flagged, grouped by category
    this.flaggedProductsByCategory = fp.flow(
      fp.filter(item => _.includes(changedProductIds, item.store_product.id)),
      fp.groupBy(item => getStoreProductCategory(item))
    )(_filterProducts(categorizedProducts))

    const productInventoryListIds = _.map(productInventoryList, 'id')

    // Loop through each changed product identified by cart compare and flag them and add message when necessary

    _.forEach(this.flaggedProductsByCategory, products => {
      _.forEach(products, product => {
        const productId = product.store_product.id

        // Flag product when it has either an inventory change, price change or product blackout.

        product.hasProductInventoryChange = _.includes(productInventoryListIds, productId)

        const productInBlackoutList = _.find(productBlackoutList, { id: productId })
        if (productInBlackoutList) {
          product.hasProductBlackout = true

          // Get the product blackout message from the product blackout list
          product.blackoutMessage = productInBlackoutList.message
        }

        const productInConfigLeadtimeList = _.find(configurableProductLeadtimeList, {
          id: productId,
        })
        if (productInConfigLeadtimeList) {
          product.hasConfigurableProductLeadtime = true

          // Get the configurable product lead time message from the associated list
          product.configurableProductLeadtimeMessage = productInConfigLeadtimeList.message
        }

        const productInPricingList = _.find(productPricingList, listObj => listObj.id === productId)
        if (productInPricingList) {
          product.hasProductPricingChange = true

          // Set new price and old price from the productPricingList
          product.newPrice = productInPricingList.new_price
          product.oldPrice = productInPricingList.old_price
        }
      })
    })

    this.hasFlaggedProductsByCategory = _.keys(this.flaggedProductsByCategory).length > 0

    // Get the offers associated with the offer id's that come from the compare cart offers list

    if (offersList && offersList.length > 0) {
      $q.all(offersList.map(offer => offersService.getOffer(offer.id)))
        .then(data => {
          this.flaggedOffers = data

          this.hasFlaggedOffers = this.flaggedOffers && this.flaggedOffers.length > 0

          // Figure out what compare cart notice to use if there are flagged offers
          if (this.hasFlaggedProductsByCategory) {
            if (this.hasFlaggedOffers) {
              this.compareCartNotice = lang.values.compareCartNoticeProductsAndOffers
            } else {
              this.compareCartNotice = lang.values.compareCartNoticeProducts
            }
          } else if (this.hasFlaggedOffers) {
            this.compareCartNotice = lang.values.compareCartNoticeOffers
          }
        })
        .catch(_ => {
          this.hasFlaggedOffers = false

          // Figure out what compare cart notice to use if there are no flagged offers
          if (this.hasFlaggedProductsByCategory) {
            this.compareCartNotice = lang.values.compareCartNoticeProducts
          }
        })
    } else {
      this.hasFlaggedOffers = false

      // Figure out what compare cart notice to use if there are no flagged offers
      if (this.hasFlaggedProductsByCategory) {
        this.compareCartNotice = lang.values.compareCartNoticeProducts
      }
    }
  },
})
