import angular from 'angular'
import debounce from 'lodash/debounce'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { SHOPPING_SELECTOR_STAGES } from '@app/angular/constants/shoppingSelectorStages'
import parse from '@app/utils/parseV2ContextParam'

/**
 * User can be forced into a certain store/context through URL override context params
 *
 * NOTE: See ALLOWED_OVERRIDES below for currently supported overrides
 *
 * The format should be like this (you can combine, using "context" for defaults):
 *  - "?context=mode:delivery,location.zip:26201,..."
 *  - "?context_trial=mode:pickup,location.store:34,..."
 *  - "?context_authed=mode:delivery,location.name:26201,..."
 *
 * Override names (e.g. mode:...,location.zip:...) must be defined in ALLOWED_OVERRIDES below
 *
 * This provides a more simple alternative to userOverridesService
 * It leverages the the shopping context modal selector to do the work
 * This means that as we change the way context works, this won't break,
 * which should hopefully make this code much easier to maintain
 */

/** Constants for override key names, exported */
export const URL_OVERRIDES = {
  MODE: 'mode',
  DISMISS_INITIAL_MODALS: 'dismissInitialModals',
}

/** The URL can contain params: context_trial, context_authed and/or context (for defaults) */
const PARAMS = {
  CONTEXT: 'context',
  CONTEXT_TRIAL: 'context_trial',
  CONTEXT_AUTHED: 'context_authed',
}

/** Allowed override options (anything else will be filtered out) */
const ALLOWED_OVERRIDES = {
  [URL_OVERRIDES.MODE]: String, // The mode to choose in the shopping selector
  /* TODO Future options for this (to be implemented)
    'location.zip': String, // The zip to use on the location step
    'location.store': Number, // The store id to use
    'location.name': String, // The store name to use
    time: Number, // The timeslot to select as an offset in hours from now
    closeable: Boolean, // [true|false] Show close button on context select when it shows up
    forceClose: Boolean, // [true|false] Close the shopping selector even if not complete after this
    dismissCartCompare: Boolean, // [true|false] Auto-accept the result of any cart compare
  */
  [URL_OVERRIDES.DISMISS_INITIAL_MODALS]: Boolean, // [true|false] Close sign up modals, any other modals before this
}

/** User Overrides V2 Service */
export default angular
  .module('userOverridesV2ServicesModule', [])
  .service(
    'userOverridesV2Service',
    function userOverridesV2Service(
      $q,
      $rootScope,
      $timeout,
      $uibModalStack,
      auth,
      shoppingSelectorService
    ) {
      const service = this
      let capturedParams = null // Initially null to indicate not yet captured

      /**
       * A list of valid url param names we're watching for
       *
       * Used by app.js to remove the params from the url after capture
       */
      service.PARAM_NAMES = Object.values(PARAMS)

      /**
       * STEP 1
       * Pre-captures and records context params from the url (if any) for later usage
       * Note: See top comment for url param formatting
       *
       * Called in app.js at the point the router map params
       * (This potentially gets fired multiple times, so once we collect, we ignore any excess calls)
       *
       * @param {object} paramObject  - The object of urls params and their values
       */
      service.preCaptureContextParamOverrides = paramObject => {
        const defaults = parse(paramObject, PARAMS.CONTEXT, ALLOWED_OVERRIDES)
        const trial = parse(paramObject, PARAMS.CONTEXT_TRIAL, ALLOWED_OVERRIDES)
        const authed = parse(paramObject, PARAMS.CONTEXT_AUTHED, ALLOWED_OVERRIDES)

        capturedParams = capturedParams || {} // Shows that this got called at least once

        if (defaults || trial) {
          capturedParams.trial = { ...defaults, ...trial }
        }
        if (defaults || authed) {
          capturedParams.authed = { ...defaults, ...authed }
        }
      }

      /**
       * Helper function - Listen for auth events, set up actions and call debounced callback
       *
       * @param {function} callback    - Callback once intent is present
       *                                 (Call the passed param to stop listening)
       * @param {function} [firstCall] - Optional: Always called on the first auth event triggered
       */
      service.onAuthReady = (callback, firstCall = null) => {
        const callbackDebounced = debounce(destroyListenerCallback => {
          if (auth.getContext()?.intent !== undefined) {
            callback(destroyListenerCallback)
          }
        }, 100)

        let hasDoneInitialCall = false
        const destroyListenerCallback = $rootScope.$on('auth', () => {
          if (!hasDoneInitialCall && firstCall) {
            hasDoneInitialCall = true
            firstCall()
          }
          callbackDebounced(destroyListenerCallback)
        })
      }

      /**
       * STEP 2
       * This listens for the a successful intent and handles opening the shopping context
       * after all modals have been closed
       */
      service.initializeContextParamOverrideSystem = () => {
        if (capturedParams === null) {
          console.error(`Must first use "userOverridesV2Service.preCaptureContextParamOverrides"
          before using "userOverridesV2Service.initializeContextParamOverrideSystem"`)
          return
        }

        const onAuthCall = destroyListenerCallback => {
          // Once there is an intent and there is no open modal
          if (!$uibModalStack.getTop()) {
            destroyListenerCallback()
            const overrides = service.selectOverrides()
            if (Object.values(SHOPPING_MODES).includes(overrides[URL_OVERRIDES.MODE])) {
              shoppingSelectorService.suppressAnimationOnNextOpen()
              shoppingSelectorService.open(SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT)
            } else if (overrides[URL_OVERRIDES.MODE]) {
              console.error(`Context param in URL must have valid "mode" override!
              Found value: ${overrides[URL_OVERRIDES.MODE]}`)
            }
          } else {
            waitForAllModalsClosed().then(() => onAuthCall(destroyListenerCallback))
          }
        }

        service.onAuthReady(onAuthCall, () => {
          // Dismiss all initial modals if requested
          const initialOverrides = capturedParams[auth.user.status === 'trial' ? 'trial' : 'authed']
          if (initialOverrides?.[URL_OVERRIDES.DISMISS_INITIAL_MODALS]) {
            $uibModalStack.dismissAll()
            service.deleteOverride(URL_OVERRIDES.DISMISS_INITIAL_MODALS)
          }
        })
      }

      /**
       * Waits for all the modals to be closed via promise chaining
       * This is a horrible piece of code all because $uibModalStack does not provide access to the
       * stack. Whomever wrote $uibModalStack at Angular has unfortunately made it difficult to use.
       *
       * @returns {Promise}
       */
      function waitForAllModalsClosed() {
        return $q(resolve => {
          // Timeout to wait for the modal to close and be popped off fo the modal stack
          $timeout(() => {
            const topModal = $uibModalStack.getTop()
            if (!topModal) {
              resolve()
            } else {
              // Somehow the promises for the modal don't work when fetched this way. This is so
              // backwards but there's no other way to detect a modal closing, so here we are <_<
              $uibModalStack
                .getTop()
                .value.modalScope.$on('modal.closing', () => resolve(waitForAllModalsClosed()))
            }
          })
        })
      }

      /**
       * STEP 3
       * Used by the context selector to check and get overrides for automated application
       * SIDE EFFECT NOTE: Removes whichever context mode params are not applicable (trial vs auth)
       *
       * @returns {object}
       */
      service.selectOverrides = () => {
        const isUserTrial = auth.user.status === 'trial'
        return capturedParams[isUserTrial ? 'trial' : 'authed'] || {}
      }

      /**
       * STEP 4
       * Used by the context selector to removes whichever context mode params are not applicable
       * I.e. Trial vs Authed
       *
       * @returns {object}
       */
      service.removeNonApplicableOverrides = () => {
        const isUserTrial = auth.user.status === 'trial'
        capturedParams[isUserTrial ? 'authed' : 'trial'] = {}
      }

      /**
       * STEP 5
       * Used by the context selector to indicate that overrides have been processed
       * @returns {null}
       */
      service.deleteOverride = key => {
        const isUserTrial = auth.user.status === 'trial'
        const overrides = capturedParams[isUserTrial ? 'trial' : 'authed']
        if (overrides) {
          delete overrides[key]
        }
      }
    }
  )
