import _ from 'lodash'
import angular from 'angular'
import { USER_PAYMENT_ACTION } from '@app/angular/services/modules/analytics/analyticsConstants'
import { PAYMENT_CONTEXTS, PAYMENT_TYPES } from '@app/angular/constants/payments'

const EVENT_TRIGGER_DELAY = 250

export default angular
  .module('CheckoutPaymentControllerModule', [])
  .controller(
    'CheckoutPaymentController',
    function (
      $filter,
      $q,
      $rootScope,
      $scope,
      $state,
      $timeout,
      $window,
      Restangular,
      addressList,
      analytics,
      auth,
      cartService,
      checkoutAnalytics,
      checkoutService,
      config,
      errorModalService,
      lang,
      mobileService,
      payment,
      paymentGateway,
      reservationService,
      subscriptionsService
    ) {
      Object.assign(
        $scope,
        { auth, cartService, config, payment, reservationService, addressList },
        {
          giftCard: {},
          cartIsValidated: false,
          tipIsValid: true,
          form: {},
        }
      )

      // In this case of modify order, set the previous payment instrument into payment
      const modifiedOrder = cartService.cart?.order
      const existingPaymentInstrument = modifiedOrder?.payment_instruments?.[0]
      const existingPaymentType = modifiedOrder?.payment_type

      $scope.paymentContext = PAYMENT_CONTEXTS.CHECKOUT

      if (existingPaymentType && payment) {
        payment.payment_type = existingPaymentType
        payment[existingPaymentType] = existingPaymentInstrument
      }

      // Extend the payment object if needed
      if (!$scope.payment.gift_cards) {
        $scope.payment.gift_cards = []
      }

      /**
       * This method executes everytime a new card is added
       *
       * @returns { undefined }
       */
      $scope.checkoutNewCreditCard = success => {
        $timeout(() => {
          const { payment } = $scope

          checkoutAnalytics.addPaymentOption({
            payment,
            success,
            user_action: USER_PAYMENT_ACTION.PAYMENT_CHANGE,
          })
        }, EVENT_TRIGGER_DELAY)
      }

      $scope.onShowCardForm = () => {
        checkoutAnalytics.addCreditCardOpen()
      }

      cartService
        .fetch()
        .then(() => $scope.validateCart())
        .then(validated => ($scope.cartIsValidated = validated || $scope.cartIsValidated))

      $scope.validateCart = () =>
        cartService.validateCart({
          cart_id: cartService.getCartId(cartService.getCurrentCartType()),
        })

      // Fetch Subscriptions

      subscriptionsService.fetch().then(subscriptions => {
        $scope.subscriptions = subscriptions

        // Generate a label for subscriptions which includes the price

        $scope.subscriptionLabel = function (product) {
          const currentSubscription = product || $scope.subscription
          if (currentSubscription) {
            return `${currentSubscription.name} For ${$filter('currency')(
              currentSubscription.base_price
            )}`
          }
        }

        // Check whether user has one of the subscription products in their cart
        $scope.getSubscription = function () {
          let found = null
          _.each($scope.subscriptions, subscription => {
            const item = cartService.getItem(subscription)
            if (item) {
              found = item
            }
          })
          return found ? found.store_product : null
        }

        // Select a subscription by default

        $scope.subscription = $scope.getSubscription() || subscriptions[0]
      })

      $scope.loadImage = function (paytype) {
        return require(`static/images/payment-${paytype}`)
      }

      // Add a gift card

      $scope.addGiftCard = function () {
        if (!$scope.giftCard.giftCardNumber || !$scope.giftCard.giftCardAmount) {
          errorModalService.open('You must enter a gift card and an amount', 'OK')
          return
        }

        if (
          config.giftCardLength &&
          $scope.giftCard.giftCardNumber.length !== config.giftCardLength
        ) {
          errorModalService.open(`Gift card must be ${config.giftCardLength} characters.`, 'OK')
          return
        }

        // Make a clone so we can add the card temporarily in order to sync
        // and then remove from the frontend pending being returned again be the
        // backend after being validated

        const giftCardsClone = angular.copy($scope.payment.gift_cards)

        $scope.payment.gift_cards.push({
          amount: $scope.giftCard.giftCardAmount,
          card_number: $scope.giftCard.giftCardNumber,
        })

        // Push the new gift card to the backend

        $scope.payment.put().then(
          response => {
            $scope.payment.gift_cards = response.gift_cards // Replace the local cards list with the reponse version

            delete $scope.giftCard.giftCardAmount
            delete $scope.giftCard.giftCardNumber

            checkoutAnalytics.addPaymentOption({
              payment: $scope.payment || {},
              success: true,
              user_action: USER_PAYMENT_ACTION.GIFT_CARD_CHANGE,
            })
          },
          response => {
            errorModalService.open(response.data ? response.data.message : response.message, 'OK')
            checkoutAnalytics.addPaymentOption({
              payment: $scope.payment || {},
              success: false,
              user_action: USER_PAYMENT_ACTION.GIFT_CARD_CHANGE,
            })
          }
        )

        // Revert to the cloned card list

        $scope.payment.gift_cards = giftCardsClone
      }

      // Remove a gift card

      $scope.removeGiftCard = function (index) {
        $scope.payment.gift_cards.splice(index, 1)
        $scope.payment.put()
      }

      // If this is a reorder with a previous gift card, ensure that it is applied.
      $scope.$watch(
        () => cartService.lastUpdated,
        (_newVal, _oldVal) => {
          if (cartService.cart && cartService.cart.order && cartService.cart.order.gift_cards) {
            const giftCards = cartService.cart.order.gift_cards
            let promise = $q.all([])
            _.forEach(giftCards, giftCard => {
              // $timeout is needed to pass a potential array of unique gift cards through the input
              promise = promise.then(() => {
                return $timeout(() => {
                  $scope.giftCard.giftCardAmount = giftCard.amount.toString()
                  $scope.giftCard.giftCardNumber = giftCard.card_number.toString()
                  $scope.addGiftCard()
                }, 2000)
              })
            })
          }
        }
      )

      $scope.getPaymentTypes = () => {
        const paymentTypes =
          auth.user.store.payment_types[reservationService.reservation?.timeslot.fulfillment_type]

        if (mobileService.isMobileApp() || window.inAppBrowser) {
          return paymentTypes.filter(p => p !== PAYMENT_TYPES.PAYPAL)
        }

        return paymentTypes
      }

      //
      //  Payment Functions
      //

      let gatewayService

      /**
       * Updates the payment type
       * @param   { boolean }  logAnalytics:     Optional: True if logging the edit_payment event
       * @returns { undefined }
       */

      $scope.updatePaymentType = function (logAnalytics = false) {
        gatewayService = paymentGateway.getService($scope.payment.payment_type)
        if (logAnalytics) {
          try {
            checkoutAnalytics.editPaymentOptions({
              payment: $scope.payment,
              paymentLabel:
                lang.values.paymentTypes[$scope.payment.payment_type][
                  reservationService.reservation.timeslot.fulfillment_type
                ],
              success: true,
              user_action: USER_PAYMENT_ACTION.PAYMENT_CHANGE,
            })
          } catch (e) {}
        }

        $scope.paymentTypeValid = gatewayService.status // gatewayService.update is an object -> {ready: T/F} so that we can get that inheritance magic
        $scope.selectedGateway = paymentGateway.getTemplate($scope.payment.payment_type)
      }

      // Set `$scope.payment.payment_type` if there is only one possible type.
      const paymentTypes = auth.user.store.payment_types[auth.getContext().intent]
      if (paymentTypes && paymentTypes.length === 1) {
        $scope.payment.payment_type = paymentTypes[0]
      }

      if ($scope.payment.payment_type) {
        $scope.updatePaymentType()
      }

      // Post and validate checkout
      $scope.checkout = function () {
        $scope.waitingForCheckout = true
        $rootScope.isAppLoading = true

        return checkoutService
          .performCheckout($scope.payment, gatewayService, {
            cart_id: cartService.getCartId(cartService.getCurrentCartType()),
          })
          .catch(() => {
            $scope.waitingForCheckout = false
            $rootScope.isAppLoading = false
          })
      }

      $scope.addSubscription = subscription => {
        cartService.addItem(subscription).then(cartService.validateCart)
        $scope.subscription = subscription
      }

      $scope.paymentPageCompletion = function () {
        $scope.waitingForCheckout = true // Lock the Order button, this is currently the first function it hits.
        if (config.enableBillingInformation && $scope.payment.payment_type === 'auth_capture') {
          $scope.submitBillingInformation()
        } else {
          $scope.checkout()
        }
      }

      // if the context is delivery ensure the customers account has been linked to instacart
      $scope.hasICAccountLinked = () => {
        if (
          auth.user &&
          auth.user.status !== 'trial' &&
          auth.getContext().intent === 'delivery' &&
          config.instacart.enableAccountLinking
        ) {
          return auth.user.ic_linked
        }
        return true
      }

      $scope.hasFailedLoadingMultipleDeliveryAddresses = () => {
        return (
          config.enableMultipleDeliveryAddresses &&
          auth.user.context.intent === 'delivery' &&
          !addressList.addressesLoaded
        )
      }

      $rootScope.$on('reservationExpired', () =>
        $state.go('main.checkout.reservation.confirmation')
      )

      $scope.shouldShowPaymentWarning = () =>
        Boolean(lang.values?.paymentWarning && lang.values?.paymentWarning !== '')
    }
  )
