import angular from 'angular'
import rxUpsellModal from '@app/angular/components/rxUpsell/rxUpsellModal.html'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { OneCartActions } from '@app/services/oneCart/types'
import { auth as authService } from '@instacart/enterprise-services'

export default angular
  .module('rxUpSellServiceModule', [])
  .service(
    'rxUpSellService',
    function rxUpSellService(
      $rootScope,
      $state,
      $uibModal,
      $window,
      analytics,
      auth,
      catalogCategoriesService,
      config,
      confirmationModal,
      lang,
      oneCartService,
      registrationService
    ) {
      const service = this
      // determine if its from RX SSO flow
      service.rxSSO = false

      service.open = (removeRxItemsFromCart, cartServiceFetch, rxDeliverableContext) => {
        analytics.logSegmentEvent('rx.upsell_modal_triggered', {
          is_rx_sso: service.rxSSO,
          upsell_modal_enabled: config.enableRxUpsellModal,
        })
        if (config.enableRxUpsellModal && service.rxSSO) {
          // reset the flag
          service.fromRxSSO(false)

          return $uibModal.open({
            template: rxUpsellModal,
            controller: [
              '$scope',
              $scope => {
                $scope.close = () => {
                  analytics.logSegmentEvent('rx.upsell_groceries_continue', { success: true })
                  // changes zip code to match with Rx item
                  auth
                    .setContext(rxDeliverableContext)
                    .then(() =>
                      auth.update({
                        store_id: rxDeliverableContext.storeNumber,
                        has_changed_store: true,
                      })
                    )
                    .then(() => cartServiceFetch())
                    .then(() => auth.sendUserUpdatedStore(auth.user))
                    .then(() => catalogCategoriesService.fetchCategories(true))
                  $scope.$close()
                }
                $scope.continue = () => {
                  analytics.logSegmentEvent('rx.upsell_checkout_continue', { success: true })
                  return authService.api.auth
                    .getOneCartCheckoutRedirect()
                    .then(resp => {
                      oneCartService.redirectToPBI(resp.location, resp.auth_token, {
                        action: OneCartActions.CHECKOUT,
                      })
                    })
                    .catch(resp => {
                      if (resp && resp.error === HTTP_ERRORS.PII_UNAUTHORIZED) {
                        $scope.$close()
                        // If PII has timed out
                        registrationService.promptLogin(
                          $state.current.name,
                          $state.params,
                          '',
                          '',
                          lang.values.loginToContinue,
                          LOGIN_CONTEXTS.REDIRECT_ONECART_CHECKOUT
                        )
                      }
                    })
                }
              },
            ],
            backdrop: 'static',
            keyboard: true,
          }).result
        }

        return Promise.resolve()
      }

      service.fromRxSSO = sso => (service.rxSSO = sso)
    }
  )
