import { lang as langService } from '@instacart/enterprise-services'

/**
 * Validates the `giftCardLengths` config is correct and that it's not in conflict with `giftCardLength`
 *
 * @param {object}                      config           - Retailer configs
 * @param  {number|undefined}            config.giftCardLength   - Original old giftCardLength config
 * @param  {array<number>|undefined}     config.giftCardLengths  - New multi giftCardLengths config
 */
export const validateGiftCardConfigs = function validateGiftCardConfigs(config) {
  const { giftCardLength, giftCardLengths } = config

  if (giftCardLength && giftCardLengths)
    console.warn('Conflict: "giftCardLengths" config takes precedence over "giftCardLength"')

  if (
    giftCardLengths &&
    (!Array.isArray(giftCardLengths) || giftCardLengths.some(len => typeof len !== 'number'))
  )
    console.error('Invalid config: "giftCardLengths" should be an array of numbers')
}

/**
 * Checks if the gift card number and amount are valid
 *
 * @param {object}                      config           - Retailer configs
 * @param  {number|undefined}            config.giftCardLength   - Original old giftCardLength config
 * @param  {array<number>|undefined}     config.giftCardLengths  - New multi giftCardLengths config
 * @param  {array<number>|undefined}     config.lag              - The lang configs
 *
 * @param {object}                      giftCard         - The gift card information
 * @param  {string}                      giftCard.card_number     - The card number string
 * @param  {string}                      giftCard.amount          - The card amount string
 *
 * @returns {{isValid: boolean, message: (string|null)}}
 */
export const validateGiftCard = function validateGiftCard(config, giftCard) {
  const { amount, card_number: cardNumber } = giftCard || {}
  const { giftCardLength, giftCardLengths, lang } = config
  const { hasGiftCardLengths, isGiftCardLengthRange } = produceGiftCardFacts(giftCardLengths)

  const hasInvalidFields = !cardNumber || !amount
  if (hasInvalidFields) return { isValid: false, message: lang.giftCard.invalidFields }

  const hasBadLength = hasGiftCardLengths
    ? !giftCardLengths.includes(cardNumber?.length)
    : giftCardLength && cardNumber?.length !== giftCardLength
  if (hasBadLength) {
    const message = langService.interpolate(lang.giftCard.invalidLength, {
      giftCardLength: hasGiftCardLengths
        ? isGiftCardLengthRange
          ? `${giftCardLengths[0]} - ${giftCardLengths[giftCardLengths.length - 1]}`
          : giftCardLengths.sort().join(` ${lang.or} `)
        : giftCardLength,
    })

    return { isValid: false, message }
  }

  return { isValid: true, message: null }
}

/**
 * @private
 * Takes in the `giftCardLengths` and produces facts about how the config is defined
 *
 * @param {array<number>|void|unknown} giftCardLengths  - The `giftCardLengths` retailer config
 * @returns {{isGiftCardLengthRange: boolean, hasGiftCardLengths: boolean}}
 */
function produceGiftCardFacts(giftCardLengths) {
  const hasGiftCardLengths = Array.isArray(giftCardLengths) && giftCardLengths.length > 0

  const isGiftCardLengthRange = Boolean(
    hasGiftCardLengths &&
      giftCardLengths.length >= 2 &&
      giftCardLengths.every((current, idx, arr) => (idx > 0 ? arr[idx - 1] === current - 1 : true))
  )

  return { hasGiftCardLengths, isGiftCardLengthRange }
}
