import angular from 'angular'
import { redirectToPBI, oneCartEnabled } from '@app/services/oneCart/utils'
import { OneCartActions } from '@app/services/oneCart/types'

export default angular
  .module('oneCartModule', [])
  .service('oneCartService', function oneCartService($rootScope, addressList, mobileService) {
    /**
     * This code fixes an issue with Safari mobile that renders cached pages when users click on the back button.
     * While this provides a better experience for users, it becomes a problem with dynamic pages.
     *
     * This event is only triggered when the page is loaded, that means the callback won't execute when users press
     * the back button inside the app
     *
     * Fix was taken from https://stackoverflow.com/questions/8788802/prevent-safari-loading-from-cache-when-back-button-is-clicked/13123626#13123626
     */
    const refreshOnBackButton = () => {
      if (mobileService.isMobile && !mobileService.isMobileApp()) {
        window.onpageshow = function (event) {
          if (event.persisted) {
            $rootScope.reloadState()
          }
        }
      }
    }

    // TODO: we can't completely refactor this as we need the $rootscope and address list refactored out
    this.redirectToPBI = (
      location,
      auth_token = '',
      { action = OneCartActions.CHECKOUT, method = 'POST', orderId }
    ) => {
      const postCloseRefresh = () => {
        $rootScope.isAppLoading = true

        // Refetch the delivery addresses in case they were modified while checking out.
        addressList.fetchAddresses()

        $rootScope.$broadcast('cartRefresh', () => {
          if (mobileService.isMobileApp()) $rootScope.isAppLoading = false
        })
      }

      const result = redirectToPBI(location, auth_token, { action, method, orderId })
        .then(response => {
          refreshOnBackButton()
          return response
        })
        .then(action === OneCartActions.LOGOUT ? x => x : postCloseRefresh)

      // Hide cart after the InAppBrowser shows.
      $rootScope.$broadcast('sideCart', 'close')

      return result
    }

    // this should just use the one cart hook when react refactor is done (est. completion 2025)
    this.enabled = () => oneCartEnabled()
  })
