import _ from 'lodash'
import angular from 'angular'
import isMobileDevice from '@app/utils/isMobile'

export default angular
  .module('langServicesModule', [])
  .provider('lang', function lang(configConstants) {
    const provider = this

    provider.values = configConstants.lang // TODO: Handle multiple languages
    provider.aria = configConstants.langAria
    provider.errorMessages = configConstants.langErrorMessages

    provider.$get = [
      '$interpolate',
      '$sanitize',
      '$sce',
      'config',
      function ($interpolate, $sanitize, $sce, config) {
        const service = this

        service.values = angular.copy(provider.values)
        service.aria = _.assign(angular.copy(provider.values), angular.copy(provider.aria))
        service.errorMessages = angular.copy(provider.errorMessages)

        service.safe = function (value) {
          return $sce.trustAsHtml(value)
        }

        service.getValue = function (key) {
          return key.split('.').reduce((prev, curr) => {
            return prev ? prev[curr] : undefined
          }, configConstants.lang)
        }

        /**
         * TODO:
         * Memoize
         * Default to template as string
         */

        service.template = function (str, values) {
          return $sce.trustAsHtml(service.templateAsString(str, values))
        }

        service.pluralize = (key, count) => {
          const keyPlural = `${key}Plural`

          if (count === 1) {
            return key
          }

          if (keyPlural in service.values) {
            return service.values[keyPlural]
          }

          return service.values[key]
        }

        service.templateAsString = function (str, values) {
          // Replace ES6-style formatted strings with angular expressions
          const expStr = str ? str.replace(/\$\{([^}]+)\}/g, '{{$1}}') : ''

          // Interpolate with the provided values, allowing HTML injection
          const exp = $interpolate(expStr)

          return exp(values)
        }

        service.sanitize = $sanitize

        service.getErrorMessage = code => {
          // Make sure it's a string
          const errorKey = `${code}`
          const { errorMessages } = service
          return (
            (isMobileDevice(config) && errorMessages?.mobile?.[errorKey]) ||
            errorMessages?.default?.[errorKey]
          )
        }

        return service
      },
    ]
  })
