import angular from 'angular'
import { angularIsReactRoute } from '@app/react/routes'

export default angular
  .module('ReactStorefrontBuilderModule', [])
  .directive('reactStorefrontBuilder', function () {
    // react-storefront-builder is used to determine where to append element
    // (attrs.reactStorefrontBuilder)
    return function (scope, element, attrs) {
      // TODO: when we make homepage composable for everyone, remove this condition
      if (angularIsReactRoute()) {
        element[0].parentNode.removeChild(element[0])
        document.getElementById(attrs.reactStorefrontBuilder)?.appendChild(element[0])

        scope.$on('$destroy', () => {
          element.remove()
        })
      }
    }
  })
