import angular from 'angular'

import { utils as timeslotUtils } from '@instacart/enterprise-timeslots'
import { DEFAULT_TIMESLOT_DATE_FORMAT } from '@app/angular/constants/timeslots'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { IC_PLACEMENTS } from '@app/angular/constants/express'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { CHECKOUT_STEP_IDS } from '@app/angular/constants/checkout'

import template from './scheduleForm.html'
import weightDriftModal from './views/weightDriftModal.html'

class ScheduleForm {
  constructor(
    $rootScope,
    $state,
    $timeout,
    $uibModalStack,
    auth,
    cartService,
    checkoutAnalytics,
    checkoutService,
    config,
    errorModalService,
    express,
    imageURLService,
    lang,
    mobileService,
    registrationService,
    reservationService,
    shoppingSelectorService,
    utilService
  ) {
    const { intent, user } = auth
    const { delivery, pickup } = config.shoppingSelector
    const { enableAccountLinking } = config.instacart
    const { icExpress } = config
    const { loadStaticImage } = utilService

    Object.assign(this, {
      $rootScope,
      $state,
      $timeout,
      $uibModalStack,
      auth,
      cartService,
      checkoutAnalytics,
      checkoutService,
      config,
      enableAccountLinking,
      errorModalService,
      express,
      icExpress,
      imageURLService,
      intent,
      lang,
      loadStaticImage,
      mobileService,
      registrationService,
      reservationService,
      shoppingSelectorService,
      user,
    })

    this.services = { reservationService }
    this.showContextChangeBtn = delivery.exists || pickup.exists
  }

  showTimeslotPlacement() {
    return this.user.ic_linked && this.express.isPlacementConfigEnabled(IC_PLACEMENTS.TIMESLOT)
  }

  showFulfillmentBanner() {
    return (
      !this.showTimeslotPlacement() &&
      this.intent.delivery() &&
      this.config.shoppingSelector.delivery.fulfilledByInstacart
    )
  }

  /**
   * Changes the section to edit mode by calling the
   * edit prop function
   *
   * @returns { undefined }
   */
  enterEditMode() {
    const { $timeout } = this
    this.edit({ step: CHECKOUT_STEP_IDS.SCHEDULE })
    $timeout(() => this.logViewEvent())
  }

  /**
   * Is triggered by <timeslot-selectors> when a time slot has been selected, and logs this CHANGE event
   * @param {boolean} success - Whether updating to the new timeslot was successful
   *
   * @returns { undefined }
   */
  onDoSave({ success }) {
    if (success) {
      this.onSave()
    }

    this.checkoutAnalytics.editTimeslotOptions({
      reservation: this.reservationService.reservation,
      success,
    })
  }

  /**
   * Is triggered by <timeslot-selectors> when the timeslot component is loading
   *
   * @returns { undefined }
   */
  onLoad() {
    this.loadedSuccess = null
  }

  /**
   * Is triggered by <timeslot-selectors> when the timeslot component has loaded, showing timeslot events
   *
   * @returns { undefined }
   */
  onLoadDone() {
    this.loadedSuccess = true
    this.logViewEvent()
  }

  /**
   * Is triggered by <timeslot-selectors> when the timeslot component has failed to load
   *
   * @returns { undefined }
   */
  onLoadError(response) {
    const { $state, auth, edit, errorModalService, lang, registrationService } = this
    const currentState = $state.current.name

    this.loadedSuccess = false
    this.logViewEvent()

    if (currentState === 'main.checkout.checkoutPage') {
      const { error, message, payload } = response?.data || {}
      const errorMessage = message || response?.message

      const invalidItemIds = (payload?.weight_drift_item_ids || []).map(id => id.toString())
      const invalidItems = this.cartService
        .getItems()
        .filter(item => invalidItemIds.includes(item.store_product.id))

      const step =
        auth.getContext().intent === SHOPPING_MODES.DELIVERY
          ? CHECKOUT_STEP_IDS.DELIVERY
          : CHECKOUT_STEP_IDS.PICKUP

      switch (error) {
        case HTTP_ERRORS.EXT_FULFILLMENT_PROVIDER_ERROR:
          // pop a lite version of the cart compare modal
          if (invalidItems.length) {
            return errorModalService.openCustom(weightDriftModal, {
              items: invalidItems,
              imageURLService: this.imageURLService,
              message: errorMessage,
              onClose: () => this.$rootScope.reloadState(),
            })
          }

          return errorModalService.open(errorMessage, lang.values.ok).then(() => edit({ step }))
        case HTTP_ERRORS.SESSION_EXPIRED_ERROR:
        case HTTP_ERRORS.PII_UNAUTHORIZED:
        case HTTP_ERRORS.MISSING_CART:
          return errorModalService
            .open(lang.values.expiredTokenErrorMessage, 'OK', null)
            .then(() =>
              registrationService.promptLogin(currentState, '', '', '', lang.values.loginToContinue)
            )
            .then(() => {
              /*
               * The need to login again is a rare edge case. We do this to resolve two problems:
               *  1. For timeslots, Connect does not seem to reload session info for social login
               *  2. Timeslots state gets in a confused state when logged out
               */
              window.location.reload()
            })
        default:
        // Timeslot timeout error, let user just try again
      }
    }
  }

  /**
   * Is triggered by enterEditMode, onLoadDone and onLoadError - logs a VIEW event
   * The event is only logged if the timeslot is already loaded or failed to load, otherwise it waits
   *
   * @returns { undefined }
   */
  logViewEvent() {
    const { checkoutAnalytics, editMode, loadedSuccess } = this

    if (editMode && loadedSuccess !== null) {
      checkoutAnalytics.viewTimeslotOptions({ success: loadedSuccess })
    }
  }

  onAfterCartCompare(shouldRecalculateFees) {
    this.checkoutService.handleCartCompareChange(shouldRecalculateFees)
  }

  getSelectedCheckoutAddress() {
    return this.checkoutService.deliveryAddress
  }
}

export default angular
  .module('ScheduleFormModule', [])
  .component('scheduleForm', {
    bindings: {
      editMode: '<',
      onError: '&',
      onSave: '&',
      edit: '&',
      hideChevron: '<',
    },
    template,
    controller: ScheduleForm,
  })
  .filter('reservationScheduleToString', (config, reservationService) => {
    return reservation => {
      return timeslotUtils.getMainLabel(reservation, {
        format: reservationService.reservation
          ? config.apiDateFormat
          : DEFAULT_TIMESLOT_DATE_FORMAT,
      })
    }
  })
