import angular from 'angular'

export default angular
  .module('IwsRegulationAlertModalServiceModule', [])
  .service(
    'iwsRegulationAlertModalService',
    function iwsRegulationAlertModalService($uibModal, $window, auth, config, localStorageService) {
      const service = this

      service.open = function (product) {
        auth.user.status === 'trial'
          ? $window.sessionStorage.setItem('firstIWSAlcoholicProductAdded', true)
          : localStorageService.setItem('firstIWSAlcoholicProductAdded', true)
        return $uibModal.open({
          template:
            '<react-iws-regulation-alert-modal on-cancel="(cancel)" on-continue="(continue)" store-product="product" />',
          controller: [
            '$scope',
            'cartService',
            ($scope, cartService) => {
              $scope.continue = () => {
                $scope.$close()
              }
              $scope.cancel = () => {
                cartService.updateItemQuantity(product, 0)
                $scope.$close()
              }
              $scope.product = product
            },
          ],
          backdrop: 'static',
          keyboard: true,
        }).result
      }
    }
  )
