import angular from 'angular'
import template from './sideCartSortModal.html'

export default angular
  .module('sideCartSortModalModule', [])
  .controller('sideCartSortModal', function ($scope, $window, auth, modalOptions) {
    $scope.sortOptions = modalOptions.sortOptions
    $scope.sort = modalOptions.sort || null

    $scope.$watch('sort', (newVal, oldVal) => {
      if (newVal !== oldVal) {
        $scope.apply()
      }
    })
    $scope.apply = function () {
      $scope.$close({
        sort: $scope.sort,
      })
    }
  })

  .service('sideCartSortModalService', function sideCartSortModalService($uibModal) {
    /**
     * Opens a side cart sort modal dialog.
     * @param {ModalOptions} options Options to pass to modal
     * @return {Promise<FilterResults>} The new filter options to be used
     */
    this.open = function (options) {
      return $uibModal.open({
        template: template,
        controller: 'sideCartSortModal',
        resolve: {
          modalOptions: function () {
            return options
          },
        },
      }).result
    }

    return this
  })
