// These are the checkout steps for the old checkout flow and the new checkout flow
// old checkout flow: EVENTS.CART.VIEW -> EVENTS.CHECKOUT.ENTER_UPSELL -> EVENTS.CHECKOUT.VIEW_FULFILLMENT
// -> EVENTS.CHECKOUT.CONFIRM_FULFILLMENT -> EVENTS.CHECKOUT.VIEW_PAYMENT
// new checkout flow: EVENTS.CART.VIEW -> EVENTS.CHECKOUT.ENTER_UPSELL -> EVENTS.CHECKOUT.ENTER_REVIEW
export const CHECKOUT_STEPS = {
  [EVENTS.CART.VIEW]: 1,
  [EVENTS.CHECKOUT.ENTER_UPSELL]: 2,
  [EVENTS.CHECKOUT.ENTER_REVIEW]: 3,
  [EVENTS.CHECKOUT.VIEW_FULFILLMENT]: 3,
  [EVENTS.CHECKOUT.CONFIRM_FULFILLMENT]: 4,
  [EVENTS.CHECKOUT.VIEW_PAYMENT]: 5,
}

/*
 * Defines the keys of sources that wil be passed to ANALYTICS
 */
export const SOURCES = {
  ANALYTICS_SERVICE: 'analyticsService',
  CART: 'cart',
  CONTACT: 'contact',
  CHECKOUT_ID: 'checkoutUUID',
  OFFER: 'offer',
  OFFER_ORIGIN: 'offerOrigin',
  ORDER: 'order',
  PAYMENT: 'payment',
  RESERVATION: 'reservation',
  SUCCESS: 'success',
  TIMESLOT: 'timeslot',
  TIP: 'tip',
  UPSELL_ITEMS: 'upsellProducts',
  URL_SLUG: 'urlSlug',
  USER: 'user',
}

/*
 * A map of constants that can be used in the analytics functions for checkoutAnalytics
 * They take in sources object and pass out an object from the input
 * @Requirements(req1, req2, ...) is used for verification
 */
class CART_ANALYTICS {
  @Requirements(SOURCES.ANALYTICS_SERVICE, SOURCES.CART)
  CART_ID = sources => ({ cart_id: getCartId(sources[SOURCES.CART]) })

  @Requirements(SOURCES.ANALYTICS_SERVICE, SOURCES.CART)
  INFO_ANALYTICS = sources => ({
    cart: sources[SOURCES.ANALYTICS_SERVICE].getCartInfoAnalytics(sources[SOURCES.CART]),
  })

  @Requirements(SOURCES.CART)
  ITEM_DETAILS = sources => ({ item_details: getItemDetails(sources[SOURCES.CART]) })

  @Requirements(SOURCES.USER)
  INTENT = sources => ({ fulfillment: sources[SOURCES.USER]?.context?.intent })
}

class CHECKOUT_ANALYTICS {
  @Requirements(SOURCES.USER)
  CONTEXT = sources => ({ context: getContextDetails(sources[SOURCES.USER]) })

  @Requirements(SOURCES.ANALYTICS_SERVICE, SOURCES.ORDER)
  ORDER = sources => ({
    order: getOrderDetails(sources[SOURCES.ANALYTICS_SERVICE], sources[SOURCES.ORDER]),
  })

  @Requirements(SOURCES.PAYMENT)
  PAYMENT = sources => ({ payment: getPaymentDetails(sources[SOURCES.PAYMENT]) })

  @Requirements(SOURCES.USER)
  SHOP = sources => ({ shop: getShopDetails(sources[SOURCES.USER]) })

  @Requirements(SOURCES.TIP)
  TIP = sources => ({ tip: getTipDetails(sources[SOURCES.TIP]) })

  @Requirements(SOURCES.ANALYTICS_SERVICE, SOURCES.UPSELL_ITEMS)
  UPSELL_ITEMS = sources => ({
    upsell_products: sources[SOURCES.ANALYTICS_SERVICE].getProductListAnalytics(
      sources[SOURCES.UPSELL_ITEMS]
    ),
  })
}

class FLOW_ANALYTICS {
  @Requirements(SOURCES.CHECKOUT_ID)
  INSTANCE = sources =>
    sources[SOURCES.CHECKOUT_ID] !== false ? { checkout_id: sources[SOURCES.CHECKOUT_ID] } : {}

  @Requirements(SOURCES.URL_SLUG)
  SOURCE_TYPE = sources => ({ source_type: getSourceType(sources[SOURCES.URL_SLUG]) })

  @Requirements(SOURCES.URL_SLUG)
  SOURCE_VALUE = sources => ({ source_value: getSourceValue(sources[SOURCES.URL_SLUG]) })
}

class OFFER_ANALYTICS {
  @Requirements(SOURCES.OFFER)
  @Plural(SOURCES.OFFER) // "offers [SOURCES.OFFER, ...]" --> "offers: [getOfferDetails, ...]"
  OFFER_DETAILS = sources => ({ offer: getOfferDetails(sources[SOURCES.OFFER]) })

  @Requirements(SOURCES.OFFER_ORIGIN)
  OFFER_ORIGIN = sources => ({ offer_via_item: getItemDetail(sources[SOURCES.OFFER_ORIGIN]) })
}

class RESERVATION_ANALYTICS {
  FULFILLMENT_DATE = sources => ({
    fulfillment_date: getFulfillmentDate(sources[SOURCES.RESERVATION]),
  })
}

class TIMESLOT_ANALYTICS {
  @Requirements(SOURCES.TIMESLOT)
  TIMESLOT_DETAILS = sources => ({ timeslot: getTimeslotDetails(sources[SOURCES.TIMESLOT]) })
}

export const ANALYTICS = {
  CART: new CART_ANALYTICS(),
  CHECKOUT: new CHECKOUT_ANALYTICS(),
  FLOW: new FLOW_ANALYTICS(),
  OFFER: new OFFER_ANALYTICS(),
  RESERVATION: new RESERVATION_ANALYTICS(),
  TIMESLOT: new TIMESLOT_ANALYTICS(),
}

