/*
 * Page slug constants for detection
 */
const PAGE = {
  CART_PAGE_URL: '/checkout/',
  CHECKOUT_FLOW_URL: '/checkout/cart',
  CART_TYPE: 'CART',
  CHECKOUT_TYPE: 'CHECKOUT',
}

/*
 * Sponsored source constants for Segment
 */
export const SEGMENT_SPONSORED_SOURCE = {
  INSTACART: 'instacart',
  DASHBOARD: 'dashboard',
  THIRD_PARTY: 'third party',
}

/**
 * Returns cart items analytics data
 *
 * @param {object} cart
 */
function getItemDetails(cart) {
  if (!cart) return

  return cart.items?.map(item => getItemDetail(item))
}

/**
 * Returns an item analytics data
 *
 * @param {object} item
 */
function getItemDetail(item) {
  if (!item) return

  const { allow_substitutions, id, type, quantity } = item

  const { brand_name: brand, categories, name, tags } = item?.store_product || {}

  return {
    allow_substitutions,
    brand,
    categories,
    id,
    name,
    quantity,
    tags,
    type,
  }
}

/**
 * Returns analytics data from a timeslot object
 *
 * @param {object} timeslot
 */
function getTimeslotDetails(timeslot) {
  if (!timeslot) return

  const {
    allow_alcohol,
    day_of_week,
    from_time,
    id,
    shop_fee,
    source,
    to_time,
    unavailable,
    within_hours,
  } = timeslot

  return {
    allow_alcohol,
    day_of_week,
    from_time,
    id,
    shop_fee,
    source,
    to_time,
    unavailable,
    within_hours,
  }
}

/**
 * Returns analytics data from an offer object
 *
 * @param {object} offer
 */
function getOfferDetails(offer) {
  if (!offer) return

  // ALL OFFER RELATED
  const { id, reward } = offer

  // CART OFFER RELATED
  const {
    description,
    name: offerName,
    offer_type: offerType,
    offer_progress: { completion_count, condition: offerCondition, reward: offerReward } = {},
  } = offer

  // PROMO OFFER RELATED
  const { condition, offer: { name, offer_type: type } = {} } = offer

  // CLIPPABLE OFFER RELATED
  const { clipped, days, limit_basket, promo_code, source, terms } = offer

  return {
    clipped,
    completion_count,
    condition: condition || offerCondition,
    description,
    days,
    id,
    limit_basket,
    name: name || offerName,
    offerReward,
    promo_code,
    reward: offerReward ? { ...reward, ...offerReward } : reward,
    source,
    terms,
    type: type || offerType,
  }
}

/**
 * Returns analytics data for page type based on url
 *
 * @param {string} urlSlug - The path section of the current url
 */
function getSourceType(urlSlug) {
  if (!urlSlug) return

  if (urlSlug.startsWith(PAGE.CHECKOUT_FLOW_URL)) {
    return urlSlug.includes(PAGE.CART_PAGE_URL) ? PAGE.CART_TYPE : PAGE.CHECKOUT_TYPE
  }
}

/**
 * Returns analytics data for page url based on source type
 *
 * @param {string} urlSlug - The path section of the current url
 */
function getSourceValue(urlSlug) {
  if (getSourceType(urlSlug) === PAGE.CHECKOUT_TYPE) {
    return urlSlug
  }
}

/**
 * Returns analytics data from a payment object
 *
 * @param {object} payment
 */
function getPaymentDetails(payment) {
  if (!payment) return

  // TODO We need to decide what kind of payment information we want to log and what we don't
  // TODO BrainTree reveals nothing much too - another reason to upgrade
  const { gift_cards = null, payment_type = null } = payment

  const giftCards = gift_cards && gift_cards?.map(({ amount }) => ({ amount }))
  return { gift_cards: giftCards, payment_type }
}

/**
 * Returns analytics data from an order object
 *
 * @param {AnalyticsServicesModule} analytics
 * @param {object} order
 */
function getOrderDetails(analytics, order) {
  if (!order) return

  const {
    customer_comment,
    customer_first_order,
    estimated_totals,
    fulfillment_date,
    fulfillment_type,
    id,
    order_items,
    payment_type,
    status,
    store,
    timeslot,
  } = order

  return {
    customer_comment,
    customer_first_order,
    estimated_totals,
    fulfillment_date,
    fulfillment_type,
    id,
    order_items: analytics.getProductListAnalytics(order_items),
    payment_type,
    status,
    store,
    timeslot,
  }
}

/**
 * Returns analytics shop data from a user object
 *
 * @param {object} user
 */
function getShopDetails(user) {
  if (!user) return

  return user?.store
}

/**
 * Returns analytics tip data from a tip object
 *
 * @param {object} tip
 */
function getTipDetails(tip) {
  if (!tip) return

  const { option, customAmount } = tip
  return { option, customAmount }
}

/**
 * Returns analytics context data from a user object
 *
 * @param {object} user
 */
function getContextDetails(user) {
  if (!user) return

  const clonedContext = { ...(user?.context || {}) }
  delete clonedContext.intent
  return clonedContext
}

/**
 * Returns cart id from the cart object
 *
 * @param {object} cart
 */
function getCartId(cart) {
  if (!cart) return

  return cart.cart_instance_id
}

/**
 * Returns the fulfillment date from the reservation object
 *
 * @param {object} reservation
 */
function getFulfillmentDate(reservation) {
  return reservation.fulfillment_date
}
