import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'
import dayjs from 'dayjs'

import { order } from '@instacart/enterprise-services'
import loadAssetImage from '@app/utils/loadAssetImage'

const PAGE_LIMIT = 20

export default angular
  .module('OrderHistoryControllerModule', [])
  .controller(
    'OrderHistoryController',
    function (
      $q,
      $scope,
      $state,
      $stateParams,
      checkoutService,
      config,
      lang,
      oneCartService,
      orderService
    ) {
      $scope.config = config
      $scope.useMostRecentPagination = oneCartService.enabled()
      $scope.orderPaginationStack = checkoutService.orderPaginationStack

      $scope.filters = [
        {
          id: '30-days',
          name: 'Past 30 Days',
          value: {
            from_date: dayjs().startOf('day').subtract(30, 'days').format(config.apiDateFormat),
            to_date: dayjs().endOf('day').format(config.apiDateFormat),
          },
        },
        {
          id: '6-months',
          name: 'Past 6 Months',
          value: {
            from_date: dayjs().startOf('day').subtract(6, 'months').format(config.apiDateFormat),
            to_date: dayjs().endOf('day').format(config.apiDateFormat),
          },
        },
      ]

      let thisYear = dayjs().year()
      while (thisYear >= config.transactionStartYear) {
        const dayjsYear = dayjs().year(thisYear)
        $scope.filters.push({
          id: thisYear,
          name: thisYear,
          value: {
            from_date: dayjsYear.startOf('year').format(config.apiDateFormat),
            to_date: dayjsYear.endOf('year').format(config.apiDateFormat),
          },
        })
        // eslint-disable-next-line no-plusplus
        thisYear--
      }

      $scope.getOrderListParams = function () {
        return {
          limit: PAGE_LIMIT,
          after: $stateParams.order_cursor,
        }
      }

      $scope.getPurchaseListParams = function () {
        return {
          limit: PAGE_LIMIT,
          after: $stateParams.purchase_cursor,
        }
      }

      $scope.selectedRange = _.find($scope.filters, dateRange => {
        return String(dateRange.id) === $stateParams.selectedRange
      })

      $scope.filterTypes = []

      if (config.orderHistory.showInStore && config.orderHistory.showOnline) {
        $scope.filterTypes.push({
          id: config.orderHistoryOptions.inStoreOnline.id,
          type: config.orderHistoryOptions.inStoreOnline.type,
          label: config.orderHistoryOptions.inStoreOnline.label,
        })
      }

      if (config.orderHistory.showInStore) {
        $scope.filterTypes.push({
          id: config.orderHistoryOptions.inStore.id,
          type: config.orderHistoryOptions.inStore.type,
          label: config.orderHistoryOptions.inStore.label,
        })
      }

      if (config.orderHistory.showOnline) {
        $scope.filterTypes.push({
          id: config.orderHistoryOptions.online.id,
          type: config.orderHistoryOptions.online.type,
          label: config.orderHistoryOptions.online.label,
        })
      }

      $scope.selectedType = _.find($scope.filterTypes, { id: $stateParams.selectedType })

      // $stateParams.selectedType could be null or set to an invalid state
      if (!$scope.selectedType) {
        $scope.selectedType = $scope.filterTypes[0]
      }
      $scope.filterByType = { type: $scope.selectedType.type }

      $scope.resetFilterState = function (selectedRange, selectedType) {
        $state.go('.', {
          selectedRange: selectedRange.id,
          selectedType: selectedType.id,
        })
      }

      $scope.nextPageOrders = function (nextOrderCursor, nextPurchaseCursor) {
        checkoutService.orderPaginationStack.push([
          $stateParams.order_cursor,
          $stateParams.purchase_cursor,
        ])
        $state.go('.', {
          order_cursor: nextOrderCursor,
          purchase_cursor: nextPurchaseCursor,
        })
      }
      $scope.prevPageOrders = function () {
        const [order_cursor, purchase_cursor] = checkoutService.orderPaginationStack.pop()
        $state.go('.', {
          order_cursor,
          purchase_cursor,
        })
      }

      $scope.getBanner = warehouseId =>
        config.multipleBanners.banners.find(banner => banner.bannerExtId === warehouseId)

      $scope.handleViewOrderClick = order => orderService.handleViewOrderClick(order)

      $scope.setFilter = function () {
        let orderQueryParams = $scope.selectedRange.value
        let purchaseQueryParams = $scope.selectedRange.value
        if ($scope.useMostRecentPagination) {
          orderQueryParams = $scope.getOrderListParams()
          purchaseQueryParams = $scope.getPurchaseListParams()
        }

        // add oneCart.enabled config to queryString
        orderQueryParams.one_cart_enabled = oneCartService.enabled()

        // add user timezone to queryString
        orderQueryParams.user_timezone = Intl.DateTimeFormat().resolvedOptions().timeZone

        const orders = order.api.orders.getAll(orderQueryParams).then(response => {
          if (response?._meta) {
            $scope.order_meta = response._meta
          }
          const { items } = response
          return _.each(items, item => {
            item.type = 'order'
            item.location = 'Online'
          })
        })

        const purchases = order.api.purchases.getAll(purchaseQueryParams).then(response => {
          if (response?._meta) {
            $scope.purchase_meta = response._meta
          }
          const { items } = response
          return _.each(items, item => {
            item.type = 'purchase'

            let location
            switch (item.source) {
              case 'online':
                location = 'Online'
                break
              default:
                location = 'In-store'
            }
            item.location = location
          })
        })

        $q.all([orders, purchases]).then(items => {
          $scope.orderHistory = fp.flow(
            fp.sortBy(item => {
              return item.fulfillment_date || item.timestamp
            }),
            fp.reverse,
            fp.map(item => {
              return { ...item, banner: $scope.getBanner(item.warehouse_id) }
            })
          )(items[0].concat(items[1]))
        })
      }

      $scope.loadImg = (image, fallback) => loadAssetImage(image, fallback)

      $scope.setFilter()
    }
  )
