import angular from 'angular'
import { user } from '@instacart/enterprise-services'

export default angular
  .module('CreditComponentModule', [])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.account.credits', {
      url: '/credits',
      views: {
        'content@main.account': {
          template: require('./credits.html'),
          controller: 'CreditsController',
          resolve: {
            credits() {
              return user.api.credits.getAll().then(response => response?.items)
            },
          },
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "creditsController", webpackPrefetch: true */ './creditsController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.availableCredits)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.availableCredits,
          },
        ],
      },
    })
  })
