import angular from 'angular'
import Cookies from 'js-cookie'
import searchSession from '@app/services/search/session'

export default angular
  .module('searchServiceModule', [])
  .service('searchService', function searchService(config, lang, mobileService) {
    const TAB_PRODUCTS = { label: lang.values.searchTabs.products, state: 'main.search' }
    const TAB_RECIPES = { label: lang.values.searchTabs.recipes, state: 'main.search.recipes' }
    const TAB_CONTENT = { label: lang.values.searchTabs.content, state: 'main.search.types' }

    this.getTypes = () => {
      const tabs = [TAB_PRODUCTS]

      if (this.isRecipesEnabled()) {
        tabs.push(TAB_RECIPES)
      }

      if (this.isDotComEnabled()) {
        tabs.push(TAB_CONTENT)
      }

      return tabs
    }

    this.startNewSearchSession = () => {
      this.snrid = searchSession.startNewSession()
      return this.snrid
    }

    // Used for sharing snrid from Dotcom user navigating to web platform via search widget.
    this.createSnridFromDotcomCookie = () => {
      this.snrid = Cookies.get('dotcomSearchId')
      searchSession.startNewSession(this.snrid)
      Cookies.remove('dotcomSearchId')
    }

    this.getSnrid = () => {
      // When the user refreshes the search results page, the id would be lost
      if (!this.snrid) {
        if (Cookies.get('dotcomSearchId')) {
          this.createSnridFromDotcomCookie()
        } else {
          this.startNewSearchSession()
        }
      }
      return this.snrid
    }

    this.getTypeBy = (value, attr = 'label') => this.getTypes().find(type => type[attr] === value)

    this.hasMultipleTypes = () => this.getTypes().length > 1

    this.isDotComEnabled = () =>
      (config.dotComModule.enableSearch && !mobileService.isMobileApp()) ||
      (config.dotComModule.enableSearchNativeMobile && mobileService.isMobileApp())

    this.isRecipesEnabled = () => config.enableRecipeSearch

    this.isPrimaryResult = item => item?.search_result?.result_set === 0 || item?.placement
  })
