import angular from 'angular'
import { FULFILLMENT_TYPES } from '@app/angular/constants/checkout'

import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { ORDER_TYPES } from '@app/angular/constants/orders'
import { OneCartActions } from '@app/services/oneCart/types'

import { auth as authService } from '@instacart/enterprise-services'

export default angular
  .module('OrderServicesModule', [])
  .service(
    'orderService',
    function orderService(
      $rootScope,
      $state,
      checkoutService,
      config,
      lang,
      localStorageService,
      oneCartService,
      registrationService
    ) {
      const service = this
      const labelMap = {
        [FULFILLMENT_TYPES.PICKUP]: lang.values.pickupFee,
        [FULFILLMENT_TYPES.DELIVERY]: lang.values.deliveryFee,
      }

      service.fulfillmentLabel = (fee, type) => {
        if (fee.meta && fee.meta.isFulfillmentFee) {
          return labelMap[type]
        }
      }

      service.handleViewOrderClick = order => {
        const isIcFulfilled = config.shoppingSelector[order.fulfillment_type]?.fulfilledByInstacart

        // For in-store orders, we take users to the Unata purchase page
        if (order.type === ORDER_TYPES.INSTORE) {
          return $state.go('main.account.orderHistory.purchase', {
            id: order.id,
          })
        }

        if (isIcFulfilled && oneCartService.enabled() && order.ic_order_redirect_id) {
          // OneCart order
          $rootScope.isAppLoading = true
          return authService.api.auth
            .getOneCartOrderRedirect(order.ic_order_redirect_id, {}, order.order_reference)
            .then(resp =>
              oneCartService.redirectToPBI(resp.location, resp.auth_token, {
                action: OneCartActions.ORDER_STATUS,
                orderId: order.ic_order_id,
              })
            )
            .catch(resp => {
              $rootScope.isAppLoading = false

              if (resp && resp.error === HTTP_ERRORS.PII_UNAUTHORIZED) {
                // If PII has timed out
                localStorage.setItem('icOrderId', JSON.stringify(`${order.ic_order_redirect_id}`))
                registrationService.promptLogin(
                  $state.current.name,
                  $state.params,
                  '',
                  '',
                  lang.values.loginToContinue,
                  LOGIN_CONTEXTS.REDIRECT_ONECART_ORDER_STATUS
                )
              }
            })
        }

        // For in-store purchases and online orders with OneCart disabled, display the order details on Unata
        $state.go('main.account.orderHistory.order', {
          id: order.id,
        })
      }
    }
  )
