// If the product is a configurable product that has pricing associated with it (hasConfigurablePricing), in the context of the cart, the
// side cart, list or pricing list screen we need to pass in the unit-price, and flag use-unit-price-for-configurable-pricing as true. This unit_price reflects any modifications that have been done to the price as a result
// of configurable product pricing. When hasConfigurablePricing is used and the unit_price is passed in, we will display just the unit_price
// and neither the base price nor the sales price nor the loyalty price.
import { shouldShowUOMSelector } from '@app/services/product/selectors'
import angular from 'angular'
import template from './productPrices.html'

// This is the angular version of packages/enterprise-ui-product-price/ProductPrices.js
export default angular
  .module('ProductPricesComponentModule', [])
  .directive('productPrices', function () {
    return {
      scope: {
        product: '<',
        unitPrice: '<?',
        showUom: '<?',
        showEbtLabel: '<?',
        loyaltyIcon: '<?',
        nonLoyaltySuffix: '<?',
        inline: '<?',
        label: '<?',
        styles: '<?',
        salePriceOverride: '<?',
      },
      template,
      controller($scope, config, lang, userService) {
        $scope.loyaltyPricingEnabled = $scope.product.loyalty_price && config.enableLoyaltyPricing
        $scope.showUom = shouldShowUOMSelector.resultFunc($scope.product, config)
        $scope.hasLoyaltyCard = userService.loyaltyCards.length > 0

        $scope.i18n = {
          loyaltyTooltip: lang.values.loyaltyPricing.priceTooltip,
          loyaltyBaseSuffix: $scope.nonLoyaltySuffix || lang.values.or,
        }
      },
    }
  })
