import angular from 'angular'
import { getConfigByRule } from '@app/services/express/utils'
import template from './icExpressSideCart.html'

class IcExpressSideCart {
  constructor(config, lang, express, $scope, utilService) {
    Object.assign(this, {
      config,
      lang,
      express,
      utilService,
    })
  }

  $onInit() {
    this.icon = this.config.icExpress.placements.sidecart.icon
  }

  getTitle() {
    const { title, titleVariants } = this.config.icExpress.placements.sidecart
    const titleVariant = getConfigByRule(this.express.expressUserConfigContext, titleVariants)
    return this.title || titleVariant || title
  }

  getSubtitle() {
    const {
      membershipSubtitle,
      subtitleVariants,
      trialSubtitle,
    } = this.config.icExpress.placements.sidecart
    const subtitleVariant = getConfigByRule(this.express.expressUserConfigContext, subtitleVariants)
    const trialEligibleSubtitle =
      this.express.userCurrentSubscription && this.express.isTrialEligible() && trialSubtitle

    return this.subtitle || subtitleVariant || trialEligibleSubtitle || membershipSubtitle
  }
}

export default angular
  .module('IcExpressSideCartComponentModule', [])
  .component('icExpressSideCart', {
    bindings: {
      title: '@',
      subtitle: '<',
      enableRetailerTheme: '<',
      onClicked: '&',
      onClosed: '&',
    },
    template,
    controller: IcExpressSideCart,
  })
