import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'
import {
  SHOPPING_MODES,
  CART_COMPARE_CHANGES,
  CART_COMPARE_TRIGGERS,
} from '@app/angular/constants/context'
import template from './views/compareCartModal.html'

export default angular
  .module('CompareCartServiceModule', [])
  .service(
    'compareCartService',
    function compareCartService(
      $q,
      $state,
      $timeout,
      $uibModal,
      Restangular,
      addressList,
      auth,
      cartService,
      config,
      eComLightService,
      shoppingContextService
    ) {
      const service = this

      const modalOpen = function ({ disableClose, customCartCompare }) {
        const modal = $uibModal.open({
          template,
          controller: 'CompareCartModalController',
          keyboard: !disableClose,
          backdrop: disableClose ? 'static' : true,
          resolve: {
            customCartCompare,
          },
        }).result

        // This focus needs to be set manually because it opens on top of the
        // shopping context modal. The $timeout exists to ensure that the focus
        // will be set only after the digest cycle completes
        $timeout(() => {
          // eslint-disable-next-line no-unused-expressions
          document.querySelector('.compare-cart-modal .compare-cart-content')?.focus()
        })

        return modal
      }

      const _getCustomCartCompareConfig = (changeIn, options) => {
        const change =
          shoppingContextService.selectedFulfillmentType !== auth.getContext().intent
            ? CART_COMPARE_CHANGES.FULFILLMENT
            : changeIn
        const customCompareConfig =
          config.customCartCompare &&
          config.customCartCompare.changeIn &&
          config.customCartCompare.changeIn[change]

        if (customCompareConfig && change === CART_COMPARE_CHANGES.FULFILLMENT) {
          const entry =
            customCompareConfig.find(
              c =>
                c.from.includes(options.additionalInfo.from) &&
                c.to.includes(options.additionalInfo.to) &&
                options.additionalInfo.changes.filter(x => c.triggeredBy.includes(x)).length
            ) || {}

          return {
            ...entry,
            change,
          }
        }
      }

      const _getDeliveryAddressParams = params => {
        const context = auth.getContext()

        return (
          params.delivery_address ||
          addressList.addressItems.find(address => address.id === auth.user.context.address_id) || {
            city: context.city || '',
            address_id: context.address_id || '',
            address1: context.street_address || '',
            province: context.province_short || '',
            postal_code: context.deliveryPostalCode || '',
          }
        )
      }

      // Call compareCart API, check if there has been a change in inventory, product price or product blackout.
      // The canProceed parameter is a flag, determining if we alow the user to continue in the application flow through the modal,
      //  or if they get stopped when they respond to the modal.
      // The changeIn parameter is a string that defines whether the compare is evaluating a change in 'fulfillment', store', 'timeslot', 'reservation', or 'address'
      // In params, 'store_id' parameter is needed if the changeIn is type 'store',
      //  'timeslot' and 'fulfillment_date' are needed if changeIn is of type 'timeslot',
      //  'delivery_postal_code' is needed if changeIn is of type 'address',
      //  'fulfillment_type' (representing the "to" fulfillment type) is needed if changeIn is of type 'fulfillment', and
      //  no params are sent if changeIn is of type 'reservation'.

      service.compareCart = function (canProceed, changeIn, params = {}) {
        // Current number of categorized products in user's list/cart (this will exclude custom and uncategorized products)
        const categorizedProducts = cartService.getAllStoreProductsInCart()
        const numCategorized = categorizedProducts.length
        const numCustom = cartService.countCustomItems()
        const numTotal = numCategorized + numCustom

        // (The following assumes compareCart never gets called if the new selected context is the same as the previous one, or else this this won't be accurate)
        const betweenCartAndList =
          changeIn === CART_COMPARE_CHANGES.FULFILLMENT ||
          auth.user.context.intent === SHOPPING_MODES.INSTORE_LIST ||
          params.fulfillment_type === SHOPPING_MODES.INSTORE_LIST

        // The following gets passed to cart/list migration to ensure we show an accurate message for the user
        const transferData = {
          numCustom,
          numTotal,
          // numTransferable (defined below)
          // transferableProducts (defined below)
        }

        if (
          changeIn === CART_COMPARE_CHANGES.FULFILLMENT ||
          auth.getContext().intent !== shoppingContextService.selectedFulfillmentType
        ) {
          transferData.additionalInfo = {
            to: params.fulfillment_type,
            from: auth.user.context.intent,
          }
        }

        // Check to see if the catering shopping selector exists, otherwise fallback to eComLight.fulfillmentType
        const isFulfillmentTypeCatering =
          params.fulfillment_type ===
          (config.shoppingSelector?.catering?.exists
            ? SHOPPING_MODES.CATERING
            : config.eComLight?.fulfillmentType)

        // Note: Cart compare is ignored in catering mode, while switching stores or switching contexts.
        // This can be revisited when mixed-bag use case is considered.
        const shouldIgnoreCartCompare =
          numCategorized === 0 ||
          (config.disableCompareCart && !betweenCartAndList) ||
          (config.eComLight.enabled &&
            (eComLightService.inEComLightMode ||
              isFulfillmentTypeCatering ||
              transferData?.additionalInfo?.to === SHOPPING_MODES.CATERING ||
              transferData?.additionalInfo?.from === SHOPPING_MODES.CATERING))

        if (shouldIgnoreCartCompare) {
          // Retailers with disabled compare cart only require a backend call when there are categorized products in the cart and the user
          //  is going between list and cart, because we need to show the user an accurate message for the list/cart migration
          transferData.numTransferable = 0
          transferData.transferableProducts = []
          return $q.resolve(transferData)
        }

        const cartOrList =
          auth.user.context.intent === SHOPPING_MODES.INSTORE_LIST ? 'user_lists/default' : 'cart'

        params.delivery_address = _getDeliveryAddressParams(params)

        // Final endpoint is based on whether or not cart or list needs to be compared (cartOrList),
        //  and what change prompted the cart compare (changeIn)
        return Restangular.all(cartOrList)
          .all('compare')
          .all(changeIn)
          .post(params)
          .then(lists => {
            const hasInStoreMarkup = config.hasInStoreMarkup && betweenCartAndList

            // The lists parameter is the different lists that get received from the compare cart API.
            const unavailableCategorizedProductIds = fp.flow(
              fp.union(lists.configurable_product_leadtime),
              fp.union(lists.product_inventory),
              fp.uniqBy(item => item.id),
              fp.map(item => item.id)
            )(lists.product_blackout)
            const numUnavailable = unavailableCategorizedProductIds.length

            const hasPriceChanged = lists.product_pricing?.length > 0 || hasInStoreMarkup
            const hasOffersChanged = lists.offers?.length > 0
            const hasAvailabilityChanged = numUnavailable > 0

            const shouldShowCartCompare =
              hasAvailabilityChanged || hasPriceChanged || hasOffersChanged
            const shouldRecalculateFees = hasPriceChanged || hasAvailabilityChanged

            const changes = [
              hasPriceChanged && CART_COMPARE_TRIGGERS.PRICE,
              hasAvailabilityChanged && CART_COMPARE_TRIGGERS.STOCK,
              hasOffersChanged && CART_COMPARE_TRIGGERS.OFFER,
            ]

            transferData.additionalInfo = {
              ...transferData.additionalInfo,
              changes: changes.filter(c => c),
            }

            if (config.customCartCompare.enabled) {
              transferData.customCartCompare = _getCustomCartCompareConfig(changeIn, transferData)
            }

            transferData.shouldRecalculateFees = shouldRecalculateFees
            transferData.numTransferable = numCategorized - numUnavailable
            transferData.transferableProducts = categorizedProducts.filter(
              product => !_.includes(unavailableCategorizedProductIds, product.id)
            )

            // Allowing the cart compare list, and canProceed to be accessible outside of the service

            service.cartComparelists = lists
            service.canProceed = canProceed

            const modalOptions = config.customCartCompare.enabled
              ? {
                  customCartCompare: _getCustomCartCompareConfig(changeIn, transferData),
                }
              : {}

            if (
              shouldShowCartCompare &&
              !config.disableCompareCart &&
              (config.customCartCompare.enabled || !betweenCartAndList) &&
              $state.current.name !== 'cartCompare'
            ) {
              const dismissHandler = () => ({ ...transferData, dismissed: true })
              const openModal = () => {
                return modalOpen({
                  ...modalOptions,
                  disableClose: true,
                }).then(() => transferData, dismissHandler)
              }

              if (!modalOptions.customCartCompare) {
                return openModal()
              }

              // Checking if the cart/list has changes that are not handled by the active
              // customCartCompare rule, if there's any, we open the modal, otherwise, we proceed
              // by resolving the cart *transferData*
              const cartChanges = transferData.additionalInfo.changes
              const cartCompareTriggers = new Set(modalOptions.customCartCompare.triggeredBy)
              const changesDiff = new Set(
                [...cartChanges].filter(change => !cartCompareTriggers.has(change))
              )

              return changesDiff.size === 0 ? $q.resolve(transferData) : openModal()
            }
            return $q.resolve(transferData)
          })
          .catch(e => console.error(e))
      }
    }
  )
