import angular from 'angular'

export const configurablePayeezy = config => {
  function identify(e) {
    var t = {
      visa: /^4[0-9]*$/,
      mastercard: /^(?:5[1-5]|2[2-7])[0-9]*$/,
      'American Express': /^3[47][0-9]*$/,
      diners: /^3(?:0[0-5]|[68][0-9])[0-9]*$/,
      discover: /^6(?:011|5[0-9]{2})[0-9]*$/,
      jcb: /^(?:2131|1800|35\d{3})\d*$/,
    }
    for (var n in t) {
      if (t[n].test(e)) {
        return n
      }
    }
  }

  function t() {
    var e = []
    var t = document.getElementsByTagName('input')
    var n = document.getElementsByTagName('select')
    for (var i = 0; i < t.length; i++) {
      var r = t[i]
      var s = r.getAttribute('payeezy-data')
      if (s) {
        e[s] = r.value
      }
    }
    for (var i = 0; i < n.length; i++) {
      var r = n[i]
      var s = r.getAttribute('payeezy-data')
      if (s) {
        e[s] = r.value
      }
    }
    return e
  }
  return {
    identify: identify,
    createToken: function (e) {
      this['clientCallback'] = e
      var r = t()
      var i = 0
      var s = {}
      var o = 0
      var u = []
      if (!this.apikey) {
        i = 400
        u[o] = {
          description: 'Please set the API Key',
        }
        o++
      }
      if (!this.js_security_key) {
        i = 400
        u[o] = {
          description: 'Please set the js_security_key',
        }
      }
      if (!this.ta_token) {
        i = 400
        u[o] = {
          description: 'Please set the ta_token',
        }
      }
      if (!this.auth) {
        i = 400
        u[o] = {
          description: 'Please set auth value',
        }
      }
      if (u.length > 0) {
        s['error'] = {
          messages: u,
        }
        e(i, s)
        return false
      }
      if (config.env.mode == 'prod') {
        var a =
          'https://' +
          config.firstData.url +
          '/v1/securitytokens?apikey=' +
          this.apikey +
          '&js_security_key=' +
          this.js_security_key +
          '&callback=Payeezy.callback&auth=' +
          this.auth +
          '&ta_token=' +
          this.ta_token +
          '&type=FDToken&credit_card.type=' +
          this.clean(r['card_type']) +
          '&credit_card.cardholder_name=' +
          r['cardholder_name'] +
          '&credit_card.card_number=' +
          r['cc_number'] +
          '&credit_card.exp_date=' +
          this.clean(r['exp_month']) +
          this.clean(r['exp_year']) +
          '&credit_card.cvv=' +
          r['cvv_code'] +
          '&billing_address.zip_postal_code=' +
          r['zip_postal_code']
      } else {
        var a =
          'https://' +
          config.firstData.url +
          '/v1/securitytokens?apikey=' +
          this.apikey +
          '&js_security_key=' +
          this.js_security_key +
          '&callback=Payeezy.callback&auth=' +
          this.auth +
          '&ta_token=' +
          this.ta_token +
          '&type=FDToken&credit_card.type=' +
          this.clean(r['card_type']) +
          '&credit_card.cardholder_name=' +
          r['cardholder_name'] +
          '&credit_card.card_number=' +
          r['cc_number'] +
          '&credit_card.exp_date=' +
          this.clean(r['exp_month']) +
          this.clean(r['exp_year']) +
          '&credit_card.cvv=' +
          r['cvv_code'] +
          '&billing_address.zip_postal_code=' +
          r['zip_postal_code']
      }
      var f = document.createElement('script')
      f.src = a
      document.getElementsByTagName('head')[0].appendChild(f)
    },
    setApiKey: function (e) {
      this['apikey'] = e
    },
    setJs_Security_Key: function (e) {
      this['js_security_key'] = e
    },
    setTa_token: function (e) {
      this['ta_token'] = e
    },
    setAuth: function (e) {
      this['auth'] = e
    },
    callback: function (e) {
      this['clientCallback'](e.status, e.results)
    },
    clean: function (e) {
      let cleaned = e.split(':')
      if (cleaned[1]) {
        return cleaned[1]
      } else {
        return e
      }
    },
  }
}
