import angular from 'angular'
import { isReactDemo } from '@app/utils/isReactDemo'
import { formatImagePlacementForAnalytics } from '@app/utils/placements'

export default angular
  .module('UnataProductCollectionModule', [])
  .component('unataProductCollection', {
    bindings: {
      collection: '<',
      enablePlacements: '<',
      fullWidth: '<',
      location: '@',
      layout: '<',
      hideBorder: '<',
      attributeAddToCartEvents: '<',
      hideViewMore: '<',
    },
    controller(config, lang, mobileService, placementsService) {
      this.mobileColumnLayout = false
      this.config = config
      this.enableReactCollection = this.config.react.enableProductCollection || isReactDemo()

      // Specific configurations for existing layouts
      // This is a way to move towards generic props (similar to React)
      if (this.layout) {
        if (this.layout === 'mobile-column') {
          this.mobileColumnLayout = true
        }

        if (this.layout === 'mobile-row') {
          this.hideBorder = true
          this.fullWidth = true
        }
      }

      if (!this.location) this.location = 'Homepage Collection'
      this.srefParams = {
        collection_id: this.collection.id,
        ...(this.attributeAddToCartEvents && {
          source: this.location,
        }),
      }

      this.isMobile = mobileService.isMobile
      this.lang = lang
      this.isFull = Boolean(this.fullWidth)
      this.hasMore = this.collection.items.length < this.collection.item_count
      this.hideViewMore = Boolean(this.hideViewMore)
      this.enableFullTileOnMobile = config.enableFullTileOnMobileCollections

      this.handleImagePlacementClick = (placement, index) => {
        const formattedPlacement = formatImagePlacementForAnalytics(placement, 'collection_banner')
        placementsService.handleImagePlacementClick(
          formattedPlacement,
          'collection carousel',
          index
        )
      }
    },
    template: require('./productCollection.html'),
  })
