import { ShoppingListItem } from '@app/types/shoppingList'
import * as lang from '@instacart/enterprise-services/lang'
import { isProductAvailable } from '@instacart/enterprise-services/product/utils'
import { useLang } from '@app/services/config/hooks'
import {
  getMinimumCartSpend,
  areAllItemsRx,
  getRemainingAmount,
} from '@instacart/enterprise-services/shopping-list/_common/utils'
import { Banner } from '@app/types/config'
import cloneDeep from 'lodash/cloneDeep'
import { formatCurrency } from '@instacart/cocktail-utils'
import { CartTotalsForCartTypeMap, MultiCart, CartTypeUnion } from '@app/types/cart'

/**
 *  Creates an object with categories as the key and respective items in an array
 */
export const categorizeItemsByDepartment = (items: ShoppingListItem[]) => {
  const categories: { [key: string]: ShoppingListItem[] } = {}
  items.forEach(item => {
    // Custom items dont have categories and should not be included
    if (!item.store_product?.categories) return

    let category =
      item.store_product?.categories[0]?.name || lang.default.translate('uncategorizedCategory')

    if (!isProductAvailable(item.store_product)) {
      const langObj = useLang()
      category = langObj.currentlyUnavailable
    }

    if (category in categories) {
      categories[category] = [...categories[category], item]
    } else {
      categories[category] = [item]
    }
  })
  return categories
}

/**
 *  Creates an object by the different cart types with each holding the totals, items, categorized items, and the banner
 *
 */
export const getDataByCartType = (
  items: ShoppingListItem[],
  cartTotals: CartTotalsForCartTypeMap,
  banners: Banner[]
): MultiCart => {
  if (items?.length === 0 || !cartTotals) return {}

  const carts: MultiCart = cloneDeep(cartTotals)

  const getMinCartAlertMessage = (
    pre_reward_program_discount_product_total: number,
    itemsArray: ShoppingListItem[],
    bannerName = ''
  ) => {
    const cartMinimum = getMinimumCartSpend() || 0
    const remainingAmount = getRemainingAmount(
      cartMinimum,
      pre_reward_program_discount_product_total,
      itemsArray
    )

    const stringKey = bannerName
      ? 'cartMinimum.miniCartAlertTextWithBanner'
      : 'cartMinimum.miniCartAlertText'

    return !areAllItemsRx(items) && remainingAmount > 0
      ? lang.default.translate(stringKey, {
          amount: formatCurrency(cartMinimum),
          bannerName,
          remainingAmount: formatCurrency(remainingAmount),
        })
      : ''
  }

  items.forEach((item: ShoppingListItem) => {
    const { cart_type } = item

    const singleCart = carts[cart_type]
    if (singleCart?.items) {
      singleCart.items.push(item)
    } else if (singleCart) {
      singleCart.items = []
      singleCart.items.push(item)
    }
  })

  Object.entries(carts).forEach(([key, val]) => {
    if (val && val.items && val.items.length > 0) {
      // eslint-disable-next-line no-param-reassign
      val.categorizedItems = categorizeItemsByDepartment(val.items)
      // This assumes all the items in a store would have the same banner id and that banner and stores are 1:1
      const storeBanner = banners.find(
        (banner: Banner) => val.items && banner.bannerExtId === val.items[0].warehouse_id
      )
      // eslint-disable-next-line no-param-reassign
      val.banner = storeBanner
      // eslint-disable-next-line no-param-reassign
      val.cartMinMsg = getMinCartAlertMessage(
        parseFloat(val?.pre_reward_program_discount_product_total),
        val.items
      )
      // eslint-disable-next-line no-param-reassign
      val.cartMinMsgWithBanner = getMinCartAlertMessage(
        parseFloat(val?.pre_reward_program_discount_product_total),
        val.items,
        val.banner.name
      )
    } else {
      // We don't need to show if the cart has no items
      delete carts[key as CartTypeUnion]
    }
  })

  return carts
}
