// This is the root component for faceted search. It requires the array of filterable
// tags for a given client and the results of the /facets GET request.
import angular from 'angular'
import template from './facetedSearchTagCrumbs.html'

export default angular
  .module('FacetedSearchTagCrumbsModule', [])
  .component('facetedSearchTagCrumbs', {
    template,
    controller($scope, $state, $stateParams, $timeout, facetedSearchService, lang, utilService) {
      this.facetedSearchService = facetedSearchService
      this.lang = lang
      this.selectedTagCrumbs =
        $stateParams.tags || $stateParams.brand_name || $stateParams.brand_names
          ? facetedSearchService.tagCrumbsArray
          : []

      let brandNamesParam
      if ($stateParams.brand_name) {
        brandNamesParam = 'brand_name'
      } else if ($stateParams.brand_names) {
        brandNamesParam = 'brand_names'
      }

      this.removeSelectedTagCrumb = index => {
        this.selectedTagCrumbs = facetedSearchService.removeTagCrumbByIndex(index)

        const newQueryParams = facetedSearchService.generateTagCrumbsQueryParams()

        const newQueryParamsObject = {
          tags: newQueryParams.tags.length ? newQueryParams.tags : null,
          group_display_name: facetedSearchService.tagGroupDisplayNameArray,
        }

        newQueryParamsObject[brandNamesParam] = newQueryParams.brandNames.length
          ? newQueryParams.brandNames
          : null

        $state.go('.', newQueryParamsObject).then(() => {
          $timeout(() => {
            const previousIndex = index !== 0 ? index - 1 : index
            const focusWithinElement =
              this.selectedTagCrumbs.length === 0
                ? document.querySelector('.page-header')
                : document.querySelector('faceted-search-tag-crumbs')
            utilService.focusWithin(focusWithinElement, previousIndex)
          })
        })
      }

      this.resetSelectedTagCrumbs = () => {
        this.selectedTagCrumbs = facetedSearchService.resetTagCrumbs()
        $state
          .go('.', {
            tags: null,
            brand_name: null,
            brand_names: null,
            page: null,
            group_display_name: null,
          })
          .then(() => {
            $timeout(() => {
              const focusWithinElement = document.querySelector('.page-header')
              utilService.focusWithin(focusWithinElement)
            })
          })
      }

      this.getFacetDisplayName = name => {
        return this.facetedSearchService.getFacetDisplayName(name)
      }
    },
  })
