import _ from 'lodash'
import angular from 'angular'
import dayjs from 'dayjs'
import pluralize from 'pluralize'

export default angular
  .module('DateFiltersComponentModule', [])

  // Return the specified name for a given value from an array of
  // name/value pairs such as: [{name: "foo", value="bar"}]

  .filter('nameFromValue', function () {
    // Unused? potentially deprecate?
    return function (value, arr) {
      const obj = _.find(arr, { value })
      return obj ? obj.name : null
    }
  })

  .filter('unsafe', function ($sce) {
    return $sce.trustAsHtml
  })

  .filter('linebreaks', function ($sce) {
    return function (value) {
      if (!value) {
        value = ''
      }
      return $sce.trustAsHtml(value.replace(/[\n\r]/g, '<br>'))
    }
  })

  .filter('rootHref', function ($location) {
    return function (value) {
      // Prepend '#' when html5 mode isn't enabled (cordova)
      if (value && value.startsWith('/') && !$location.$$html5) {
        return `#${value}`
      }
      return value
    }
  })

  // Return the specified object with a given value, similar
  // to nameFromValue

  .filter('objFromValue', function () {
    // Unused? potentially deprecate?
    return function (value, arr) {
      return _.find(arr, { value })
    }
  })

  .filter('stripDecimal', function () {
    return function (value) {
      if (value) {
        return value.toString().replace(/\..+$/g, '')
      }
      return null
    }
  })

  .filter('pluralize', function () {
    return function (str, num) {
      return pluralize(str, num)
    }
  })

  .filter('date', function () {
    return function (str, format, parseFormat) {
      const day = dayjs(str, parseFormat)
      return day.isValid() ? day.format(format) : dayjs(str, 'YYYY-MM-DD').format(format)
    }
  })

  .filter('time', function () {
    return function (str, short) {
      const m = dayjs(str, 'HH:mm:ss')
      return m.format(short && !m.minutes() ? 'hA' : 'h:mmA')
    }
  })

  .filter('apiDate', function (config) {
    return function (str, format) {
      return dayjs(str, config.apiDateFormat).format(format)
    }
  })

  .filter('daysFromNow', function () {
    return function (str) {
      return dayjs(str, 'YYYY/MM/DD H:mm:ss Z').diff(dayjs(), 'days')
    }
  })

  .filter('offset', function () {
    return function (input, start) {
      start = parseInt(start, 10)
      return input.slice(start)
    }
  })

  .filter('humanBool', function () {
    return function (input) {
      return input ? 'Yes' : 'No'
    }
  })

  .filter('or', function () {
    return function (str, or) {
      return str || or
    }
  })

  .filter('capitalize', function () {
    return function (input) {
      if (!input) {
        return ''
      }
      const lowerCaseInput = input
        .trim()
        .split(' ')
        .map(word => word.toLowerCase())
        .join(' ')
      return lowerCaseInput.charAt(0).toUpperCase() + lowerCaseInput.slice(1)
    }
  })

  .filter('sortBySelected', function () {
    return (items, selectedValue, selectedObj1, selectedObj2) => {
      const sortedItems = _.orderBy(items, item => {
        return selectedObj1[item[selectedValue]] || selectedObj2[item[selectedValue]]
      })

      return sortedItems
    }
  })

  .filter('todaysHours', function () {
    return function (storeHours) {
      const todaysHoursObj = storeHours ? storeHours[dayjs().format('dddd').toLowerCase()] : null
      if (storeHours && todaysHoursObj && !_.isEmpty(todaysHoursObj)) {
        const startTime = dayjs(todaysHoursObj.start, 'HH:mm:ss')
        const endTime = dayjs(todaysHoursObj.end, 'HH:mm:ss')

        if (startTime.isValid() && endTime.isValid()) {
          return `${startTime.format('h:mm A')} - ${endTime.format('h:mm A')}`
        }
        return 'Closed'
      }
    }
  })
  .filter('reverse', function () {
    return function (items) {
      return items && items.slice().reverse()
    }
  })

  .filter('appendIfNotNull', function () {
    return function (val, toAppend) {
      return val ? val + toAppend : ''
    }
  })

  // SEO friendly urls
  .filter('slugify', function () {
    return input => {
      if (!input) {
        return
      }
      // make lower case and trim
      let slug = input.toLowerCase().trim()

      // replace invalid chars with spaces
      slug = slug.replace(/[^a-z0-9\s-]/g, ' ')

      // replace multiple spaces or hyphens with a single hyphen
      slug = slug.replace(/[\s-]+/g, '-')

      // If the last character is a hypen (was a special char) trim it
      if (slug[slug.length - 1] === '-') {
        slug = slug.substring(0, slug.length - 1)
      }

      return slug
    }
  })

  // Add http:// for absolute links

  .filter('absoluteUrl', function () {
    return url => {
      let fullUrl
      const httpProtocol = 'http://'
      const secureHttpProtocol = 'https://'

      if (url.indexOf(httpProtocol) === 0 || url.indexOf(secureHttpProtocol) === 0) {
        fullUrl = url
      } else {
        fullUrl = httpProtocol + url
      }

      return fullUrl
    }
  })

  // Find any offer which:
  // a) Has a condition related to cart total
  // b) Is related to a promo code the user has entered
  // c) Is related to an item in the cart, either a condition or a reward
  // d) Has progress toward completion

  .filter('completedOffers', function () {
    return function (allOffers, itemList) {
      return _.filter(allOffers, offer => {
        if (offer.condition.type === 'cart' || offer.promo_code) {
          return true
        }
        if (itemList && itemList.length) {
          const anyProductBelongsToOffer = _.some(itemList, item => {
            let inRewards
            const inConditions = _.find(offer.condition.products.products, {
              id: item.store_product.id,
            })

            if (offer.reward.products) {
              inRewards = _.find(offer.reward.products.products, { id: item.store_product.id })
            } else {
              inRewards = false
            }
            return inConditions || inRewards
          })
          return (
            anyProductBelongsToOffer &&
            offer.offer_progress &&
            (offer.offer_progress.condition.current_value || offer.offer_progress.completion_count)
          )
        }
        return (
          offer.offer_progress &&
          offer.offer_progress.condition &&
          (offer.offer_progress.condition.current_value > 0 ||
            offer.offer_progress.completion_count > 0)
        )
      })
    }
  })

  .filter('secondsToHoursMins', function () {
    return function (
      seconds,
      { shortenWords = false, toDateTime = false, allowSubMinuteTime = false } = {}
    ) {
      // Round up the time to 1 minute if !allowSubMinuteTime
      if (!allowSubMinuteTime && seconds < 60) {
        seconds = 60
      }

      const hours = Math.floor(seconds / 3600)
      const minutes = Math.floor((seconds % 3600) / 60)

      if (toDateTime) {
        const hoursPhrase = hours > 0 ? `${hours}H` : ''
        const minutesPhrase = minutes > 0 || (minutes === 0 && hours === 0) ? `${minutes}M` : ''

        return `PT${hoursPhrase}${minutesPhrase}`
      }
      const hoursWord = shortenWords ? 'hr' : `hour${hours === 1 ? '' : 's'}`
      const minutesWord = shortenWords ? 'm' : `min${minutes === 1 ? '' : 's'}`

      const hoursPhrase = hours > 0 ? `${hours} ${hoursWord}` : ''
      const minutesPhrase =
        minutes > 0 || (minutes === 0 && hours === 0) ? `${minutes} ${minutesWord}` : ''

      return `${hoursPhrase}${hoursPhrase && minutesPhrase ? ' ' : ''}${minutesPhrase}`
    }
  })

  .filter('telephone', function () {
    return function (input) {
      let number = input || ''

      number = number.trim().replace(/[-\s()]/g, '')

      if (number.length === 9 || number.length === 10) {
        const local = [number.substr(0, 3), number.substr(3, 3), number.substr(6)].join('-')

        return local
      }

      return number
    }
  })
