import angular from 'angular'
import { SUBSCRIPTION_STEPS } from '@instacart/enterprise-subscription-checkout'
import debounce from 'lodash/debounce'

/**
 * The outer most scope here is the below SubscriptionCheckout controller
 * The VantivIntegrationController inherits from that adding payment methods to the $scope
 * You may need to look in both controllers to find methods!
 */
const template = `
    <div ng-controller="VantivIntegrationController">
      <react-subscription-checkout
        cards="paymentInstruments"
        is-add-new-card="showAddInstrument"
        is-iframe-loading="showAddInstrumentLoader"
        is-purchasable="isPurchasable"
        asset-loading-function="(assetLoadingFunction)"
        on-state-change="(onStateChange)"
        route="$state.current.name.split('.').pop()"

        on-buy-subscription-clicked="(onBuySubscriptionClicked)"
        on-gift-subscription-clicked="(onGiftSubscriptionClicked)"
        on-selection-complete="(onSelectionComplete)"
        on-add-new-card="(addPaymentInstrument)"
        on-continue-clicked="(onContinueClicked)"
        on-delete-card="(reactMethods.onDeleteCard)"
        on-payment-iframe-rendered="(reactMethods.onPaymentIframeRendered)"
        on-purchase-complete="(onPurchaseComplete)"
        on-select-card="(reactMethods.onSelectCard)"
        on-update-card="(reactMethods.onUpdateCard)"
        selected-card-ext-id="reactMethods.getSelectedCard()"
        show-save-card-toggle="true"
      ></react-subscription-checkout>
    </div>
  `

// Function to determine whether to allow the subscription routes to be entered
const canAccess = [
  '$state',
  '$timeout',
  'configConstants',
  function ($state, $timeout, configConstants) {
    if (!configConstants.subscriptions || !configConstants.subscriptions.enabled) {
      $timeout(() => {
        $state.go('main.404')
      }, 0)
    }
  },
]

const controller = [
  '$scope',
  '$state',
  'auth',
  'analytics',
  'cartService',
  'utilService',
  function ($scope, $state, auth, analytics, cartService, utilService) {
    // This will be populated with other methods from the payment controller
    $scope.reactMethods = {}

    $scope.$state = $state
    // Add analytics tracking
    $scope.onBuySubscriptionClicked = () => {
      // TODO: remove this event hook once analytics is ported over to react
      analytics.logSegmentEvent('subscription.subscribe_self_click', {
        success: true,
      })
    }
    $scope.onGiftSubscriptionClicked = () => {
      // TODO: remove this event hook once analytics is ported over to react
      analytics.logSegmentEvent('subscription.subscribe_gift_click', {
        success: true,
      })
    }
    $scope.onSelectionComplete = ({ selectedSubscription }) => {
      // TODO: remove this event hook once analytics is ported over to react
      analytics.logSegmentEvent('subscription.select_subscription_type', {
        subscription_id: selectedSubscription.id,
        subscription_name: selectedSubscription.name,
      })
    }

    $scope.isPurchasable = !auth.user.subscription
    $scope.onContinueClicked = () => $state.go('main.home')
    $scope.assetLoadingFunction = utilService.loadAssetImage
    $scope.onPurchaseComplete = ({ selectedSubscription, path }) => {
      // TODO: remove this event hook once analytics is ported over to react
      analytics.logSegmentEvent('subscription.purchase_subscription', {
        subscription_id: selectedSubscription.id,
        subscription_name: selectedSubscription.name,
        gift: path === 'gifting',
        path: 'dedicated',
        success: true,
      })
      cartService.removeSubscriptions()
      $scope.reactMethods.onPurchaseComplete() // Provided by the payment controller
      // Need to fetch user that will now include the subscription just purchased
      auth.fetch()
    }

    // TODO "onStateChange" is being called multiple times for each route change.
    // TODO This is caused by useEffect in the React component. The proper fix requires a complete
    // TODO rethink of how to work with Angular JS routes with React.
    $scope.onStateChange = debounce(
      params => {
        if ($state.current.name.split('.').pop() !== params.currentRoute) {
          $state.go(`main.subscriptions.${params.currentRoute}`, {
            path: params.currentPath,
          })
        }
      },
      100,
      { leading: true }
    )
  },
]

export default angular.module('subscriptionCheckoutRoutesServiceModule', []).config([
  '$stateProvider',
  'langProvider',
  function ($stateProvider, langProvider) {
    $stateProvider.state('main.subscriptions', {
      url: '/subscriptions',
      resolve: {
        redirect: [
          '$timeout',
          '$state',
          ($timeout, $state) => {
            $timeout(() => {
              if ($state.current.name === 'main.subscriptions') {
                $state.go(`main.subscriptions.${SUBSCRIPTION_STEPS[0].key}`)
              }
            })
          },
        ],
        title: [
          'titleService',
          titleService => titleService.setTitle(langProvider.values.subscriptions),
        ],
        canAccess,
      },
      views: {
        'content@': {
          controller,
          template,
        },
      },
    })
    // This only exists as  the previous state won't match subscriptions with a / at the end
    $stateProvider.state('main.subscriptionsSlash', {
      url: '/subscriptions/',
      resolve: {
        redirect: [
          '$timeout',
          '$state',
          ($timeout, $state) => {
            $timeout(() => {
              if ($state.current.name === 'main.subscriptionsSlash') {
                $state.go(`main.subscriptions.${SUBSCRIPTION_STEPS[0].key}`)
              }
            })
          },
        ],
      },
    })

    // eslint-disable-next-line array-callback-return
    SUBSCRIPTION_STEPS.map(step => {
      $stateProvider.state(`main.subscriptions.${step.key}`, {
        url: step.urlName,
        data: step.data,
      })
    })
  },
])
