import angular from 'angular'
import dayjs from 'dayjs'
import _ from 'lodash'
import * as expressSelectors from '@app/services/express/selectors'
import {
  getEligibleSubscriptionPlans as getEligibleSubscriptionPlansThunk,
  getCurrentSubscription as getCurrentSubscriptionThunk,
  createSubscription as createSubscriptionThunk,
  updateSubscription as updateSubscriptionThunk,
} from '@app/services/express/thunks'

import {
  getMembershipStatus,
  isPlacementConfigEnabled,
  getStickyHeaderBanner,
  collapsePromoAndNonPromoPlans,
  getConfigByRule,
  shouldShowDynamicPlacement,
  getExpressLocalStorageKey,
  acknowledgePlacement,
} from '@app/services/express/utils'

import {
  FIRST_ORDER_KEY,
  IC_PLACEMENTS,
  MESSAGE_TYPES,
  HTML_TEMPLATES,
  MESSAGE_PLACEMENTS,
  MEMBERSHIP_STATUSES,
  EXPRESS_APPLICABLE_STATUSES,
  EXPRESS_PAYMENT_PROVIDER,
  EXPRESS_SUBSCRIPTION_STEPS,
  EXPRESS_SUBSCRIPTION_PLAN_TERMS,
} from '@app/angular/constants/express'

import { PAYMENT_PROVIDERS } from '@app/angular/constants/payments'

import { BANNER_FETCH_EVENT } from '@app/angular/services/modules/placements.service'

/**
 *
 * This service manages implements IC Express related HTTP calls
 * and also stores relevant subscription data that is used across the
 * application
 *
 */
export default angular
  .module('ExpressServicesModule', [])
  .service(
    'express',
    function express(
      $ngRedux,
      $q,
      $rootScope,
      $timeout,
      Restangular,
      auth,
      config,
      eComLightService,
      icExpressModalService,
      lang,
      localStorageService
    ) {
      const service = this

      const mapStateToService = state => ({
        userCurrentSubscription: expressSelectors.currentSubscriptionSelector(state),
        eligibleSubscriptionPlans: expressSelectors.eligibleSubscriptionsSelector(state),
        expressUserConfigContext: expressSelectors.expressUserConfigContextSelector(state),
      })

      $ngRedux.connect(mapStateToService, {
        getEligibleSubscriptionPlansThunk,
        getCurrentSubscriptionThunk,
        createSubscriptionThunk,
        updateSubscriptionThunk,
      })(service)

      const { icExpress } = config
      let promise = null
      const subscriptionPlanIdToPlanId = {}

      Object.assign(service, {
        hasLoaded: false,
        planUpdateMessage: null,
        topMessage: null,
        paymentMessage: null,
        membershipMessage: null,
        isIcExpressOptedIn: false,
      })

      service.shouldCreateSubscriptionAtCheckout = () => {
        const { optionalComponents } = config

        return optionalComponents.includes(PAYMENT_PROVIDERS) && optionalComponents.length === 1
      }

      service.init = () => {
        if (config.icExpress.enabled && auth.user?.ic_linked && auth.user?.status !== 'trial') {
          service
            .getCurrentSubscription()
            .then(service.fetchEligibleSubscriptionPlans)
            .then(() => {
              service.hasLoaded = true
              if (
                service.shouldShowDynamicPlacement(IC_PLACEMENTS.FIRST_ORDER) &&
                localStorageService.deprecatedGet(FIRST_ORDER_KEY)
              ) {
                icExpressModalService.open()
                service.acknowledgePlacement(IC_PLACEMENTS.FIRST_ORDER)
              } else if (service.shouldShowDynamicPlacement(IC_PLACEMENTS.FIRST_LOGIN)) {
                icExpressModalService.open()
                service.acknowledgePlacement(IC_PLACEMENTS.FIRST_LOGIN)
              }

              /*
               * Add listener for top banner insertion
               * When banners are fetched, add our custom header banner to the list
               */
              if (service.isPlacementConfigEnabled(IC_PLACEMENTS.STICKER_HEADER)) {
                const idx = config.icExpress.placements.stickyHeader.bannerInsertionIndex
                $rootScope.$on(BANNER_FETCH_EVENT, (event, list) => {
                  const headerBanner = getStickyHeaderBanner({
                    placementConfigEnabled: true,
                    icExpress,
                    currentSubscription: service.userCurrentSubscription,
                    expressUserConfigContext: service.expressUserConfigContext,
                  })

                  return list.splice(Math.min(idx, list.length), 0, headerBanner)
                })
              }
            })
        }
      }

      service.trialOpt = value => {
        service.isIcExpressOptedIn = value
      }

      service.getPlanIds = plan => {
        if (subscriptionPlanIdToPlanId[plan]) {
          return Promise.resolve(subscriptionPlanIdToPlanId[plan])
        }
        return this.fetchEligibleSubscriptionPlans().then(subscriptionPlans => {
          subscriptionPlans.forEach(eligiblePlan => {
            if (eligiblePlan.term_length) {
              subscriptionPlanIdToPlanId[eligiblePlan.term_length] = eligiblePlan.id
            } else {
              subscriptionPlanIdToPlanId[EXPRESS_SUBSCRIPTION_PLAN_TERMS.TRIAL] = eligiblePlan.id
            }
          })
          return subscriptionPlanIdToPlanId[plan]
        })
      }

      /**
       * Triggers loadUserCurrentSubscriptionPlan with a forced reload, returning the previous operation's result
       *
       * @returns {Promise<object>}
       */
      service.reloadUserCurrentSubscriptionPlan = previousResult => {
        return service.getCurrentSubscription(true).then(() => previousResult)
      }

      /**
       * Fetches all express subscription plans that the user qualifies for:
       *
       * @returns { Promise }
       *
       * @example Promise resolves to:
       *
       * {
       *  "subscription_plans": [
       *    {
       *      "id":1,
       *        "description": "Trial - 14 day",
       *        "price": '0.00',
       *        "currency": "USD",
       *        "polytype": "trial",
       *        "term_length": null,
       *        "value_propositions":[
       *          "Free delivery on all orders over $35 USD",
       *          "Cancel anytime within the next 14 days at no cost",
       *          "$99 USD/yr after your trial ends"
       *        ],
       *        "disclaimers":[
       *          "By signing up, you agree to the Instacart Express Terms and authorize us to charge any active payment method on file after your free trial. Cancel anytime in Your Account. Other fees, taxes, and/or tips may apply."
       *        ],
       *        "link_to_terms":"http://web.instacart.customers-stg.stg.pika.ac//terms/#terms-express"
       *    },
       *    {
       *      ...
       *    }
       *  ]
       * }
       */
      service.fetchEligibleSubscriptionPlans = async (
        forceRefresh = false,
        ignorePromoPlans = false
      ) => {
        if (!service.eligibleSubscriptionPlans || forceRefresh) {
          await service.getEligibleSubscriptionPlansThunk()
        }

        return collapsePromoAndNonPromoPlans(service.eligibleSubscriptionPlans, ignorePromoPlans)
      }

      /**
       * Fetches the user current express plan. When the user isn't subscribed to any plan.
       *
       * @param { boolean } [forceReload] Force the reloading of the current subscription
       *
       * @returns { Promise }
       *
       * @example When the user isn't subscribed to any plan the promise resolves to:
       *
       * {
       *   "eligible_for_trial": false,
       *   "was_express_member": true
       * }
       */
      service.fetchCurrentSubscriptionPlan = () => {
        return service.getCurrentSubscriptionThunk().then(({ payload }) => {
          return payload
        })
      }

      /**
       * Returns a promise with the user current express plan.
       *
       * @param {boolean} forceRefresh - when set to *true*, it will always fetch the user subscription
       *
       * @returns {Promise}
       */
      service.getCurrentSubscription = (forceRefresh = false) => {
        if (service.userCurrentSubscription && !forceRefresh) {
          return $q.resolve(service.userCurrentSubscription)
        }

        if (!forceRefresh && promise) {
          return promise
        }

        promise = service
          .fetchCurrentSubscriptionPlan(forceRefresh)
          .finally(() => (promise = undefined))

        return promise
      }

      /**
       * Subscribes the user to an Express plan
       *
       * @param { object } data
       *
       * @example data:
       *
       * {
       *   "subscription_plan_id": 1,
       *   "credit_card_id": 1234 // Optional if subscribing to trial
       * }
       *
       * @returns { Promise }
       *
       * @example promise resolves to:
       *
       * {
       *   "eligible_for_trial":false,
       *   "was_express_member":true,
       *   "current_subscription": { ... }
       * }
       */
      service.subscribe = data => {
        return service.createSubscriptionThunk(data).then(service.reloadUserCurrentSubscriptionPlan)
      }

      /**
       * Handles the flow for subscribing the user to an Express plan
       *
       * @param { object }   [paymentInstrument]   - A reference to the payment instrument used during checkout (Optional)
       * @param { function } [callback]            - A callback that is triggered no matter what (Optional)
       *                                             (The user could take many actions, hence the rationale for this)
       *
       * @returns { Promise }                      - Promise resolves if subscribed, or rejects if not
       */
      service.handleSubscribe = (paymentInstrument = null, orderId = null, callback) => {
        const isTrialEligible = this.getMembershipStatus() === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE
        const isAllowedPaymentProvider = config.optionalComponents.includes(
          EXPRESS_PAYMENT_PROVIDER
        )
        const modalInitialState = isTrialEligible
          ? EXPRESS_SUBSCRIPTION_STEPS.FORM
          : EXPRESS_SUBSCRIPTION_STEPS.AD
        const finish = () =>
          callback &&
          this.getCurrentSubscription()
            .then(callback)
            .catch(() => callback(null))

        if (isTrialEligible && isAllowedPaymentProvider && paymentInstrument?.id) {
          return this.fetchEligibleSubscriptionPlans()
            .then(subscriptionPlans => subscriptionPlans.find(x => x.term_length === null))
            .then(trialPlan =>
              this.subscribe({
                order_id: orderId,
                subscription_plan_id: trialPlan.id,
                credit_card_id: paymentInstrument.id,
              })
            )
            .catch(() => icExpressModalService.open(modalInitialState))
            .finally(finish)
        }
        // Open sign up modal for manual input of credit card information
        return icExpressModalService.open(modalInitialState).finally(finish)
      }

      /**
       * Updates the user current Express subscription
       *
       * @param { object } data
       *
       * @example data:
       *
       * {
       *   "subscription_plan_id": 50,
       *   "credit_card_id": 12324, //optional
       *   "next_subscription_plan_id": 12, // optional
       *   "cancel": true, //optional
       *   "autorenew": true, // optional
       *   "send_reminder_email": 'true', // has to be a string for some reason
       * }
       *
       * @returns { Promise }
       *
       * @example promise returns to:
       * {
       *   "eligible_for_trial":false,
       *   "was_express_member":true,
       *   "current_subscription": { ... }
       * }
       */
      service.updateSubscription = data => {
        return service.updateSubscriptionThunk(data).then(service.reloadUserCurrentSubscriptionPlan)
      }

      /**
       *
       * Toggles the users reminder on/off.
       * Will flip from on to off or off to on if 'toggle' is not passed
       * Will get the subscription id from the current subscription if no
       * subscription id is passed
       *
       * @param { string } id, optional, the subscription id
       * @param { boolean } toggle, optional, whether to turn it on or off
       *
       * @returns { Promise<Subscription> }
       *
       * @example promise subscription object:
       */
      service.toggleReminder = (id, toggle) => {
        const payload = {
          subscription_id: id || service?.userCurrentSubscription?.subscription?.id,
        }
        if (toggle === false || toggle === true) {
          payload.send_reminder_email = toggle.toString()
        } else if (service.userCurrentSubscription.subscription.send_reminder_on) {
          payload.send_reminder_email = 'false'
        } else {
          payload.send_reminder_email = 'true'
        }

        return service.updateSubscription(payload)
      }

      service.getReminderDate = () => {
        return dayjs(service.userCurrentSubscription?.subscription?.send_reminder_on)
      }

      /**
       * Tells us if the user is eligible for a trial or not
       *
       * @returns {boolean} trial eligibility
       */
      // TODO IN THE FUTURE, THIS CAN PROBABLY BE REPLACED WITH:
      // `service.getMembershipStatus() === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE`
      service.isEligibleForTrial = () => {
        return service
          .getCurrentSubscription()
          .then(() => service.userCurrentSubscription?.eligible_for_trial)
      }

      /**
       * Tells us if the user has an expired membership
       *
       * @returns {boolean} expired membership
       */
      service.isExpiredMembership = () => {
        return service
          .getCurrentSubscription()
          .then(
            () =>
              !service.userCurrentSubscription?.eligible_for_trial &&
              service.userCurrentSubscription?.was_express_member &&
              _.isEmpty(service.userCurrentSubscription?.subscription)
          )
      }

      /**
       * Determines if a placement is configured to be enabled
       *
       * @param { string } placement string
       * @returns { boolean } true means config is enabled
       */
      service.isPlacementConfigEnabled = placement => {
        return isPlacementConfigEnabled({
          placement,
          isInEcomLightMode: eComLightService.inEComLightMode,
          icExpress,
          intent: auth.getContext().intent,
          userConfigContext: service.expressUserConfigContext,
        })
      }

      /**
       * Determines if a placement should be shown based off the following:
       * - Is enabled in the config
       * - Has not be acknowledged by the user
       * - The user is not already an IC express member
       * This should be used for placements that can be acknowledged
       *
       * @param { string } placement string
       * @returns { boolean } true means visible
       */
      service.shouldShowDynamicPlacement = placement => {
        return shouldShowDynamicPlacement({
          placement,
          intent: auth.getContext().intent,
          userStatus: auth.user?.status,
          isPlacementConfigEnabled: service.isPlacementConfigEnabled(placement),
          currentSubscription: service.userCurrentSubscription,
        })
      }

      /**
       * Returns the localStorage key for a given Express *placement*
       * for the user current shopping mode
       *
       * @param { string } placement - Express placement identifier
       *
       * @returns { string }
       */
      service.getLocalStorageKey = placement =>
        getExpressLocalStorageKey(placement, auth.getContext().intent)

      /**
       * Saves to the local storage that a placement is acknowledged
       *
       * @param { string } placement string
       * @returns { undefined }
       */
      service.acknowledgePlacement = placement => {
        acknowledgePlacement(placement, auth.getContext().intent)
      }

      /**
       * Returns a membership status based on the user current subscription plan
       *
       * @returns {string}
       */
      service.getMembershipStatus = () => getMembershipStatus(this.userCurrentSubscription)

      /**
       * Returns true if the user has signed up to express
       *
       * @returns {boolean}
       */
      service.hasSubscription = () => {
        return !!this.userCurrentSubscription?.subscription?.subscription_plan
      }

      /**
       * Returns the number of remaining days of the user current subscription plan
       *
       * @returns { Number }
       */
      service.getMembershipRemainingDays = () => {
        if (!service.userCurrentSubscription) return 0

        return Math.floor(
          dayjs(service.userCurrentSubscription.subscription.end_date).diff(dayjs(), 'days')
        )
      }

      /**
       * Returns the string representing the remaining duration of the user's current subscription plan
       *
       * @returns { string }
       */
      service.getMembershipRemainingString = () => {
        return dayjs.duration(service.getMembershipRemainingDays(), 'days').humanize()
      }

      service.getMembershipEndDate = () => {
        if (
          service.getMembershipStatus() === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE ||
          !service.userCurrentSubscription
        ) {
          return dayjs().add(config.icExpress.trialDays, 'days')
        }

        return dayjs(service.userCurrentSubscription?.subscription?.end_date)
      }

      service.dismissMessage = position => {
        service[`${position}Message`] = null
      }

      service.setMessage = (message, title, type, position = MESSAGE_PLACEMENTS.TOP) => {
        service.clearMessages()
        service[`${position}Message`] = _generateMessage(message, title, type)
      }

      service.clearMessages = () => {
        service[`${MESSAGE_PLACEMENTS.TOP}Message`] = null
        service[`${MESSAGE_PLACEMENTS.PAYMENT}Message`] = null
        service[`${MESSAGE_PLACEMENTS.MEMBERSHIP}Message`] = null
      }

      service.isExpressApplicable = () => {
        return EXPRESS_APPLICABLE_STATUSES.includes(service.getMembershipStatus())
      }

      service.isTrial = () => service.getMembershipStatus() === MEMBERSHIP_STATUSES.TRIAL
      service.isCoupon = () => service.getMembershipStatus() === MEMBERSHIP_STATUSES.COUPON

      service.isTrialEligible = () =>
        service.getMembershipStatus() === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE

      service.isMember = () => service.getMembershipStatus() === MEMBERSHIP_STATUSES.MEMBERSHIP

      service.isMembershipEligible = () =>
        service.getMembershipStatus() === MEMBERSHIP_STATUSES.MEMBERSHIP_ELIGIBLE

      service.isYearlyMember = async () => {
        const planID =
          service.userCurrentSubscription &&
          service.userCurrentSubscription.subscription &&
          service.userCurrentSubscription.subscription.subscription_plan &&
          service.userCurrentSubscription.subscription.subscription_plan.id

        return (
          service.isMembershipEligible() &&
          planID === (await this.getPlanIds(EXPRESS_SUBSCRIPTION_PLAN_TERMS.YEARLY))
        )
      }

      service.isMonthlyMember = async () => {
        const planID =
          service.userCurrentSubscription &&
          service.userCurrentSubscription.subscription &&
          service.userCurrentSubscription.subscription.subscription_plan &&
          service.userCurrentSubscription.subscription.subscription_plan.id

        return (
          service.isMembershipEligible() &&
          planID === (await this.getPlanIds(EXPRESS_SUBSCRIPTION_PLAN_TERMS.MONTHLY))
        )
      }

      service.isRefundEligible = () =>
        service.isMember() && service.userCurrentSubscription.subscription.refund_eligible

      service.getNextPlan = () => {
        return (
          (service.userCurrentSubscription.subscription &&
            service.userCurrentSubscription.subscription.next_subscription_plan) ||
          {}
        )
      }

      service.getPlanType = () => {
        if (service.isTrial()) {
          return lang.values.icExpress.trial
        }

        if (service.isMember()) {
          return lang.values.icExpress.membership
        }

        return ''
      }

      service.getAdList = async () => {
        const { trial, monthly, yearly } = config.icExpress.ad.valuePropositions
        const valuePropositionsVariants = config.icExpress.ad.valuePropositionsVariants

        const valuePropositionsVariant = getConfigByRule(
          service.expressUserConfigContext,
          valuePropositionsVariants
        )

        const isYearlyMember = await service.isYearlyMember()
        const isMonthlyMember = await service.isMonthlyMember()

        if (valuePropositionsVariant) {
          return valuePropositionsVariant
        }

        if (service.isTrial() || service.isTrialEligible()) {
          return trial
        }

        if (isYearlyMember || service.isMembershipEligible()) {
          return yearly
        }

        if (isMonthlyMember) {
          return monthly
        }

        return []
      }

      /**
       * Returns the terms and conditions text based on the user status
       *
       * @returns { string }
       */
      service.getTermsAndConditions = () => {
        const { icExpress } = config
        const { label, url } = icExpress.termsAndConditions
        const { TRIAL_ELIGIBLE, TRIAL } = MEMBERSHIP_STATUSES
        const membershipStatus = service.getMembershipStatus()
        const termsAndConditions = HTML_TEMPLATES.TERMS(url, label)
        const { trialText, membershipText } = icExpress.termsAndConditions
        const { name: express, minimumOrderAmount: minOrderAmount, trialDays } = icExpress

        return lang.template(
          [TRIAL_ELIGIBLE, TRIAL].includes(membershipStatus) ? trialText : membershipText,
          {
            express,
            trialDays,
            minOrderAmount,
            termsAndConditions,
            yearlyPrice: icExpress.plans.yearly.price,
          }
        )
      }

      /**
       *
       * @param { string } message
       * @param { string } title
       * @param { string } type
       *
       * @returns { Object }
       */
      function _generateMessage(title, message, type = MESSAGE_TYPES.SUCCESS) {
        return {
          type,
          title,
          message,
        }
      }

      /**
       * Updates the user Express membership plan.
       * If the current plan is *trial*, it will set *next_subscription_plan_id*.
       * Otherwise, it will set *subscription_plan_id*
       *
       * @param {number} planId - Express plan ID
       *
       * @returns {Promise}
       */
      service.updateMembershipPlan = planId => {
        const currentSubscription = service.userCurrentSubscription?.subscription
        const { ext_payer_id, ext_instrument_id: ext_id } = currentSubscription
        const payload = {
          ext_id,
          ext_payer_id,
          subscription_id: currentSubscription?.id,
        }

        payload.next_subscription_plan_id = planId

        return service.updateSubscription(payload)
      }
    }
  )
