import angular from 'angular'
import { categoryNavBarLinksSelector } from '@app/react/components/headerDesktop/bottomRow/selectors'
import template from './categoriesMenu.html'

export default angular.module('UnataCategoriesMenuModule', []).component('unataCategoriesMenu', {
  template,
  controller(
    $ngRedux,
    $scope,
    $state,
    $window,
    lang,
    catalogCategoriesService,
    config,
    eComLightService,
    placementsService,
    utilService
  ) {
    const mapStateToService = state => {
      const {
        featuredDepartments: featuredMenu = [],
        merchandisedLandingPages = [],
        categories,
      } = categoryNavBarLinksSelector(state)
      return {
        featuredMenu,
        merchandisedLandingPages,
        categories,
      }
    }

    $ngRedux.connect(mapStateToService)(this)

    $scope.webUrl = config.env.webUrl
    $scope.categoriesMenuAdditionalLinks = config.categoriesMenuAdditionalLinks
    $scope.enableFeaturedDropdownMenu = config.enableFeaturedDropdownMenu
    $scope.isFeaturedMenuOpen = false
    $scope.lang = lang
    this.util = utilService
    this.window = $window
    $scope.toggled = isOpen => {
      $scope.isFeaturedMenuOpen = isOpen
    }

    this.isActiveCategory = categoryId =>
      // eslint-disable-next-line eqeqeq
      // eslint-disable-next-line eqeqeq
      categoryId == this.currentCategoryId || categoryId == this.currentParentCategoryId

    this.isNonEComLightCategory = category => {
      return config.eComLight.enabled ? eComLightService?.isNonEComLightCategory(category) : true
    }

    const getCategoryParentId = categoryId => {
      const category = catalogCategoriesService.categoryFromId(categoryId)
      return category && category.department && category.department.id
    }

    const updateActiveCategories = () => {
      if ($state.includes('main.shop.categories')) {
        this.currentCategoryId = $state.params.category_id
        this.currentParentCategoryId = getCategoryParentId(this.currentCategoryId)
      } else {
        this.currentCategoryId = null
        this.currentParentCategoryId = null
      }
    }

    // TODO: Get rid of this in favour of something like a resolve on the main route and a param supplied to this component
    // Wait for state changes and recalculate parent so we can apply classname to
    // parent menu item of current category if needed
    $scope.$on('$stateChangeSuccess', () => {
      updateActiveCategories()
    })

    updateActiveCategories() // On controller init: set active categories
  },
})
