import angular from 'angular'

export default angular
  .module('OrderAddOnConfirmModalControllerModule', [])
  .controller(
    'OrderAddOnConfirmModalController',
    function OrderAddOnConfirmModalController(
      $scope,
      $state,
      $stateParams,
      Restangular,
      compareCartService,
      lang,
      mobileService,
      product
    ) {
      const vm = this
      vm.product = product
      vm.order = $stateParams.order
      vm.mobile = mobileService

      $scope.loading = false

      vm.orderAddOnIncrement = () => {
        if (vm.product.quantity >= vm.product.ordering_parameters.max) {
          vm.product.quantity = vm.product.ordering_parameters.max
        } else {
          // eslint-disable-next-line no-plusplus
          vm.product.quantity++
        }

        vm.calculatePrice()
      }

      vm.orderAddOnDecrement = () => {
        // eslint-disable-next-line no-plusplus
        if (vm.product.quantity > 1) vm.product.quantity--

        vm.calculatePrice()
      }

      vm.orderAddOnUpdate = () => {
        if (vm.product.quantity >= vm.product.ordering_parameters.max) {
          vm.product.quantity = vm.product.ordering_parameters.max
        }

        vm.calculatePrice()
      }

      vm.calculatePrice = () => {
        const calculateParams = {
          product_id: vm.product.id,
          quantity: vm.product.quantity,
        }

        return Restangular.one('orders', vm.order.id)
          .one('add_on')
          .get(calculateParams)
          .then(priceResponse => {
            vm.product.unit_price = priceResponse.unit_price
            vm.product.subtotal = priceResponse.subtotal
          })
      }

      vm.confirmOrderAddOn = () => {
        const addOnPayload = {
          item_type: 'store_product',
          order_by_weight: vm.product.order_by_weight,
          quantity: vm.product.quantity,
          store_product: {
            href: vm.product.href,
            id: vm.product.id,
          },
          allow_substitutions: false,
          comment: '',
          unit_price: vm.product.uom_price.price,
          subtotal: vm.product.subtotal,
          fulfillment_type: vm.order.fulfillment_type,
        }

        $scope.loading = true

        return Restangular.one('orders', vm.order.id)
          .one('add_on')
          .customPUT(addOnPayload)
          .then(_addOnResponse => {
            $state
              .go('main.account.orderHistory.order', {
                id: vm.order.id,
                message: lang.values.orderAddOn.successMessage,
              })
              .then(() => {
                $scope.loading = false
              })
          })
          .catch(() => {
            $state
              .go('main.account.orderHistory.order', {
                id: vm.order.id,
                error: lang.values.orderAddOn.errorMessage,
              })
              .then(() => {
                $scope.loading = false
              })
          })
      }

      vm.calculatePrice()
    }
  )
