import angular from 'angular'

export default angular
  .module('headerFooterServicesModule', [])
  .run(($location, headerFooterSyncService) => {
    const searchUrlParams = $location.search()
    if (searchUrlParams?.['test-header']) {
      headerFooterSyncService.testHeaderFile = searchUrlParams['test-header']
    }
    if (searchUrlParams?.['test-footer']) {
      headerFooterSyncService.testFooterFile = searchUrlParams['test-footer']
    }
  })
  .service(
    'headerFooterSyncService',
    function headerFooterSyncService(
      $templateRequest,
      $q,
      config,
      mobileService,
      unataCommService
    ) {
      const service = this
      // let testUrlParam;
      const getHeaderSyncUrl = () => {
        let headerSyncUrl
        let defaultFileName
        if (mobileService.isMobileApp()) {
          // Return header for mobile app
          headerSyncUrl = config.dotComModule.headerSyncUrl.mobileApp
          defaultFileName = 'mobile-app-header.html'
        } else if (!mobileService.isMobileApp() && mobileService.isMobile) {
          // Return header for mobile web
          headerSyncUrl = config.dotComModule.headerSyncUrl.mobileWeb
          defaultFileName = 'mobile-web-header.html'
        } else {
          //Return header for desktop
          headerSyncUrl = config.dotComModule.headerSyncUrl.desktop
          defaultFileName = 'desktop-header.html'
        }

        if (service.testHeaderFile) {
          headerSyncUrl =
            headerSyncUrl.substring(0, headerSyncUrl.lastIndexOf('/') + 1) + service.testHeaderFile
        } else if (headerSyncUrl.endsWith('/')) {
          headerSyncUrl = headerSyncUrl + service.defaultFileName
        }

        if (headerSyncUrl && !headerSyncUrl.endsWith('.html')) {
          headerSyncUrl = headerSyncUrl + '.html'
        }

        return headerSyncUrl
      }

      service.fetchHeader = () => {
        const templateUrl = getHeaderSyncUrl()
        if (templateUrl) {
          return $templateRequest(templateUrl + '?ignoreCache=' + Date.now())
        } else {
          return $q.reject()
        }
      }

      service.fetchFooter = () => {
        let footerSyncUrl = config.dotComModule.footerSyncUrl
        if (service.testFooterFile) {
          footerSyncUrl =
            footerSyncUrl.substring(0, footerSyncUrl.lastIndexOf('/') + 1) + service.testFooterFile
        } else if (footerSyncUrl.endsWith('/')) {
          footerSyncUrl = footerSyncUrl + 'desktop-footer.html'
        }
        if (footerSyncUrl && !footerSyncUrl.endsWith('.html')) {
          footerSyncUrl = footerSyncUrl + '.html'
        }
        if (footerSyncUrl) {
          return $templateRequest(footerSyncUrl + '?ignoreCache=' + Date.now())
        } else {
          return $q.reject()
        }
      }

      service.afterHeaderLoaded = root => {
        unataCommService.send({
          eventType: unataCommService.EventType.HEADERLOADED,
          payload: {
            root,
          },
        })
      }

      service.afterFooterLoaded = root => {
        unataCommService.send({
          eventType: unataCommService.EventType.FOOTERLOADED,
          payload: {
            root,
          },
        })
      }
    }
  )
