import angular from 'angular'
import template from './savedRecipesCell.html'

export default angular.module('SavedRecipesTileModule', []).component('savedRecipesCell', {
  template,
  bindings: {
    cell: '<',
  },
  controller($scope, $state, config, savedRecipesService, mobileService) {
    const vm = this
    const thumbnailsThreshold = 4

    $scope.reactProps = {
      imagesObj: [{ loading: true }],
      border: true,
      fixedContent: false,
      imageGutter: 6,
      linkObj: {},
      titleObj: {
        iconClass: 'icon-go',
        text: vm.cell.name,
        inverted: true,
      },
    }

    if (mobileService.isMobile) {
      $scope.reactProps.linkObj.onClick = () => $state.go(vm.cell.state)
    } else {
      $scope.reactProps.linkObj.href = $state.href(vm.cell.state)
    }

    savedRecipesService.getSavedRecipes().then(savedRecipes => {
      vm.savedRecipesList = savedRecipes
      const hasMultipleImages = savedRecipes.length && savedRecipes.length >= thumbnailsThreshold

      if (hasMultipleImages) {
        $scope.reactProps.imagesObj = savedRecipes.map(recipe => ({
          src: recipe.media[0].image?.header,
        }))
      } else {
        $scope.reactProps.imagesObj = [{ icon: true, iconClass: config.savedRecipesIcon }]
      }
    })
  },
})
