import angular from 'angular'

export default angular
  .module('runtimeRoutesServicesModule', [])
  .provider('runtimeRoutes', function runtimeRoutes($stateProvider) {
    const provider = this

    // runtimeRoutes.addState will allow you to inject new states to ui-router's stateProvider
    // inside of runtime code (ie - to help in keeping API configs out of Angular .config() blocks)
    provider.$get = function () {
      return {
        addState(name, state) {
          $stateProvider.state(name, state)
        },
      }
    }
  })
