import angular from 'angular'
import { product as enterpriseProductService } from '@instacart/enterprise-services'
import template from './cartForm.html'

const DISPLAY_ITEMS = {
  MOBILE: 5,
  DESKTOP: 10,
}

class CartForm {
  constructor(
    $scope,
    cartService,
    checkoutAnalytics,
    imageURLService,
    lang,
    mobileService,
    utilService
  ) {
    Object.assign(this, {
      $scope,
      cartService,
      checkoutAnalytics,
      imageURLService,
      lang,
      isMobile: mobileService.isMobile,
      utilService,
    })
  }

  $onInit() {
    this.cartSizes = [this.items?.length]
    /**
     * Watches for cart size changes and calls the `onCartSizeChange` callback attribute
     * with the size of the change.
     */
    this.$scope.$watch(
      () => this.items?.length,
      newCartSize => {
        const lastCartSize = this.cartSizes.pop()
        const cartChangeSize = newCartSize - lastCartSize

        if (cartChangeSize !== 0) {
          this.onCartSizeChange({ cartChangeSize })
        }

        this.cartSizes.push(newCartSize)
      }
    )
  }

  /**
   * Gets the first 10 items to display
   *
   * @returns { object[] } items subset
   */
  getDisplayItems() {
    const itemsSize = this.isMobile ? DISPLAY_ITEMS.MOBILE : DISPLAY_ITEMS.DESKTOP

    return (
      this.items &&
      this.items
        .filter(
          item =>
            item?.store_product?.product_type === 'product' &&
            enterpriseProductService.utils.isProductAvailable(item.store_product)
        )
        .slice(0, itemsSize)
    )
  }

  editItems() {
    const { checkoutAnalytics } = this
    checkoutAnalytics.clickEditCart()
  }

  getCartType() {
    return this.cartService.getCurrentCartType()
  }
}

export default angular.module('CartFormModule', []).component('cartForm', {
  bindings: {
    items: '<',
    editMode: '<',
    title: '@',
    updateMessage: '<',
    onCartSizeChange: '&',
    onMessageDismiss: '&',
  },
  template,
  controller: CartForm,
})
