// Runs run-on-enter when enter key is pressed.

import angular from 'angular'

export default angular.module('runOnEnterComponentModule', []).directive('runOnEnter', function () {
  return {
    restrict: 'A',
    link(scope, el, attrs) {
      el.bind('keypress', event => {
        if (event.which === 13) {
          scope.$apply(() => {
            scope.$eval(attrs.runOnEnter)
          })
        }
      })
    },
  }
})
