// Updates the model of the form element when enter is pressed
// and removes focus.

import angular from 'angular'

export default angular
  .module('uibModalAriaLabelComponentModule', [])
  .service('uibModalAriaLabelService', function () {
    const service = this

    service.ariaLabel = ''

    service.setLabel = ariaLabel => {
      const topModalEl = document.querySelectorAll('[uib-modal-aria-label]')[0]

      service.resetLabel()
      service.ariaLabel = ariaLabel

      // Hack: Unfortunately, due to $uibModal, this is necessary because the plugin instantiates outside of our controllable scope
      // This checks to see if the modal already exists and sets aria-label if true, otherwise rely on directive to automatically set on init
      if (topModalEl) {
        topModalEl.setAttribute('aria-label', service.ariaLabel)
      }

      return service.ariaLabel
    }

    service.resetLabel = () => {
      service.ariaLabel = ''
    }
  })
  .directive('uibModalAriaLabel', function (uibModalAriaLabelService) {
    return {
      restrict: 'A',
      link(scope, el) {
        el[0].setAttribute('aria-label', uibModalAriaLabelService.ariaLabel)

        // Reset saved aria label to empty string after setting so it does not carry over to the next modal initialization
        uibModalAriaLabelService.resetLabel()
      },
    }
  })
