import angular from 'angular'
import dropdownTemplate from './configurableProductDropdown.html'

export default angular
  .module('ConfigurableProductDropdownModule', [])
  .directive(
    'configurableProductDropdown',
    function (
      $compile,
      $document,
      $state,
      $templateCache,
      $timeout,
      $window,
      auth,
      list,
      mobileService,
      productDetailsModal
    ) {
      return {
        scope: {
          product: '=',
          plain: '=',
          existingConfig: '=',
          aggregateQuantity: '=',
          location: '@',
        },
        replace: true,
        template: dropdownTemplate,
        controller($scope, $element, $attrs, lang) {
          $scope.auth = auth
          $scope.lang = lang
        },
        link(scope, element) {
          const customUpsell = 'Custom Upsell'
          const customUpsellClass = 'custom-upsell'
          const template = mobileService.isMobile
            ? $templateCache.get('configurableProductDropdown.mobile.html')
            : $templateCache.get('configurableProductDropdown.desktop.html')
          let popover
          let timeout

          scope.mobileService = mobileService
          scope.isDropdownVisible = scope.location === customUpsell ? 'hidden' : 'visible'

          // eslint-disable-next-line no-restricted-globals
          top.scope = scope

          element.on('click', e => {
            e.stopPropagation()
          })

          scope.$on('$stateChangeStart', () => {
            if (popover) {
              scope.hidePopover()
            }
          })

          scope.hidePopover = function () {
            popover.off('mouseleave')
            $document.off('click')
            popover.remove()
          }

          const getElementOffset = el => {
            const rect = el.getBoundingClientRect()
            return {
              top: rect.top + window.pageYOffset,
              left: rect.left + window.pageXOffset,
              bottom: rect.bottom,
              height: rect.height,
              right: rect.right,
              width: rect.left,
              x: rect.left + window.pageXOffset,
              y: rect.top + window.pageYOffset,
            }
          }

          scope.showPopover = function () {
            const el =
              scope.location === customUpsell
                ? document.getElementsByClassName(customUpsellClass)[0]
                : $document[0].body
            const body = angular.element(el)
            const elemPos = element[0].getBoundingClientRect()
            const topOffset = $document[0].documentElement.scrollTop || $document[0].body.scrollTop

            popover = $compile(template)(scope)
            body.append(popover)
            $timeout(() => {
              // Wait for rendering to finish
              const arrowHeight = 8
              const popoverWidth = popover[0].offsetWidth
              const popoverHeight = popover[0].offsetHeight
              let inlineStyles = {}
              let popoverClass = ''

              if (!mobileService.isMobile) {
                const offSetPostiion = getElementOffset(element[0])
                inlineStyles = {
                  top: `${elemPos.bottom + topOffset}px`,
                  left: `${offSetPostiion.left - (popoverWidth - elemPos.width) / 2}px`,
                }

                // If the popover cannot fit below, show it on top
                if (
                  elemPos.top + popoverHeight >
                  $window.innerHeight - elemPos.height - arrowHeight
                ) {
                  inlineStyles.top = `${elemPos.top - popoverHeight - arrowHeight + topOffset}px`
                  popoverClass = 'top'
                } else {
                  popoverClass = 'bottom'
                }

                popover.addClass(popoverClass)
              }

              inlineStyles.visibility = 'visible'

              popover.css(inlineStyles).on('click', e => {
                e.stopPropagation()
              })

              if (!mobileService.isMobile) {
                popover.on('mouseenter', () => {
                  $timeout.cancel(timeout)
                })

                popover.on('mouseleave', () => {
                  scope.hideDelay(400)
                })
              }

              $document.on('click', () => {
                scope.hideDelay(0)
                scope.$apply()
              })
            }, 0)
          }

          scope.hideDelay = function (delay) {
            $timeout.cancel(timeout)
            timeout = $timeout(() => {
              scope.toggle = false
              scope.hidePopover()
            }, delay)
          }

          scope.togglePopover = function () {
            scope.toggle = !scope.toggle

            if (!scope.toggle) {
              scope.hidePopover()
            } else if (scope.toggle) {
              scope.showPopover()
            }
          }

          scope.linkToProductDetails = productId => {
            productDetailsModal.open(productId, { location: scope.location })
            scope.hidePopover()
          }
        },
      }
    }
  )
  .run($templateCache => {
    $templateCache.put(
      'configurableProductDropdown.mobile.html',
      require('./configurableProductDropdown.mobile.html')
    )
    $templateCache.put(
      'configurableProductDropdown.desktop.html',
      require('./configurableProductDropdown.desktop.html')
    )
  })
