import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'
import dayjs from 'dayjs'

export default angular
  .module('purchasesCategoriesServiceComponentModule', [])
  .service(
    'purchasesCategoriesService',
    function purchasesCategoriesService(
      $q,
      $rootScope,
      $stateParams,
      Restangular,
      config,
      list,
      tagsService
    ) {
      const service = this

      $rootScope.$on('reloadState', () => {
        delete service.categories
      })

      // Fetch purchases

      service.fetch = function (refresh) {
        const deferred = $q.defer()

        const params = {
          with_ecommerce: true,
          limit: 10,
        }

        if (!refresh && service.categories) {
          deferred.resolve(service.categories)
          return deferred.promise
        }

        Restangular.all('purchases')
          .getList(params)
          .then(purchases => {
            const results = []

            // Sort purchases by date in descending order

            purchases = fp.flow(
              fp.sortBy(purchase => purchase.timestamp),
              fp.reverse,
              fp.slice(0, config.maxPreviousPurchases)
            )(purchases)

            // Populate tree objects

            _.each(purchases, purchase => {
              results.push({
                name: dayjs(purchase.timestamp, config.apiDateFormat).format('MMMM, DD YYYY'),
                state: 'main.shop.purchases.purchase',
                params: {
                  purchase_id: purchase.id,
                },

                // Add 'Add All' button to header in these views

                pageHeaderButton: {
                  label: 'Add All',
                  onclick() {
                    Restangular.one('purchases', purchase.id)
                      .get()
                      .then(order => {
                        list.loadListToCart(order.items)
                      })
                  },
                },

                sort: true,

                // Use all tags except purchased (as it's redundant here)

                tags: tagsService.tags({ value: 'purchased' }),
              })
            })

            service.categories = results
            deferred.resolve(service.categories)
          })

        return deferred.promise
      }
    }
  )
