import angular from 'angular'
import { MEMBERSHIP_STATUSES } from '@app/angular/constants/express'

import template from './icExpressCheckout.html'

class IcExpressCheckout {
  constructor($filter, config, express, lang) {
    const { icExpress } = config
    const { checkout } = icExpress.placements

    Object.assign(this, {
      lang,
      config,
      express,
      placementConfig: checkout,
      currencyFilter: $filter('currency'),
    })
  }

  $onInit() {
    this.enableRetailerTheme = this.enableRetailerTheme || this.config.icExpress.enableRetailerTheme
    this.title = this.title || this.placementConfig.title
    this.buttonLabel = this.buttonLabel || this.placementConfig.buttonLabel
    this.checkboxLabel =
      this.checkboxLabel ||
      this.lang.template(this.placementConfig.checkboxLabel, { plan: this.config.icExpress.name })
  }

  get amountForFreeDelivery() {
    const cartTotal = parseFloat(this.cartTotal)
    const freeDelivery = this.config.icExpress.minimumOrderAmount

    return freeDelivery - cartTotal
  }

  get placementTitle() {
    if (this.hasSubscription) {
      return this.lang.template(this.config.icExpress.placements.checkout.subscriptionTitle, {
        plan: this.config.icExpress.name,
      })
    }

    if (this.isTrialEligible) {
      return this.lang.template(this.config.icExpress.placements.checkout.trialEligibleTitle, {
        plan: this.config.icExpress.name,
        days: this.config.icExpress.trialDays,
      })
    }

    return this.title
  }

  /**
   * Returns whether the user has a subscription, or null if the subscription has not been acquired yet
   *
   * @returns { null | boolean }
   */
  get hasSubscription() {
    const subscription = this.userSubscription?.subscription
    if (!subscription) return null
    return !!subscription.id
  }

  /**
   * Returns whether the user is eligible for a trial
   *
   * @returns { boolean }
   */
  get isTrialEligible() {
    return this.express.getMembershipStatus() === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE
  }
}

export default angular
  .module('IcExpressCheckoutComponentModule', [])
  .component('icExpressCheckout', {
    template,
    bindings: {
      // MANDATORY
      optedIn: '=',
      onChange: '&',
      cartTotal: '<',
      userSubscription: '<',

      // OPTIONAL
      title: '@?',
      buttonLabel: '@?',
      checkboxLabel: '@?',
      enableRetailerTheme: '<?',
      termsAndConditionsUrl: '@?',
      termsAndConditionsLabel: '@?',
    },
    controller: IcExpressCheckout,
  })
