// Sets an "invalid" classname to the label of an invalid input

import angular from 'angular'

export default angular.module('LabelComponentModule', []).directive('label', $timeout => {
  return {
    restrict: 'E',
    link: function labelLink(_scope, labelElement, attrs) {
      if (attrs.for) {
        const inputElement = document.getElementById(attrs.for)
        if (inputElement && inputElement.validity) {
          const updateLabel = () => {
            if (inputElement.required) {
              labelElement.addClass('ng-required')
            } else {
              labelElement.removeClass('ng-required')
            }
          }

          $timeout(updateLabel, 0) // Wait for the rest of the DOM to populate
        }
      }
    },
  }
})
