import angular from 'angular'

export default angular
  .module('FixedToBodyComponentModule', [])
  .directive('fixedToBody', function ($document, mobileService) {
    return function (scope, element, attrs) {
      if (mobileService.isMobile || attrs.fixedToBody === 'desktop') {
        element[0].parentNode.removeChild(element[0])
        $document[0].body.appendChild(element[0])

        scope.$on('$destroy', () => {
          element.remove()
        })
      }
    }
  })
