// Detects if user tabbed into element

import angular from 'angular'

export default angular.module('tabEnterComponentModule', []).directive('tabEnter', $timeout => {
  return {
    scope: {
      tabEnter: '=',
      tabEnterFn: '&',
      tabExitFn: '&',
    },
    restrict: 'A',
    link(scope, el, attrs) {
      el.on('keyup focus', event => {
        if (event.which === 9) {
          scope.tabEnter = true
          scope.$apply()
          if (angular.isDefined(attrs.tabEnterFn)) {
            scope.tabEnterFn(event)
          }
        }
      })

      el.on('keydown blur', event => {
        if (event.which === 9) {
          scope.tabEnter = false
          scope.$apply()

          // timeout required because jqLite does not offer event delegation, so the event does not bubble up to grandparent element
          // As such, this acts as a debounce
          $timeout(() => {
            if (angular.isDefined(attrs.tabExitFn) && scope.tabEnter === false) {
              scope.tabExitFn(event)
            }
          }, 100)
        }
      })
    },
  }
})
