import angular from 'angular'
import template from './imageCell.html'

export default angular.module('ImageCellComponentModule', []).directive('imageCell', function () {
  return {
    scope: {
      url: '=',
      imageUrl: '=',
      promotedMedia: '=',
      useImageTag: '<',
    },
    replace: true,
    template,
    controller($scope) {
      $scope.imageOnly = !$scope.url
    },
  }
})
