import angular from 'angular'
import { auth as authService } from '@instacart/enterprise-services'
import { SSO_PROVIDERS } from '@app/angular/constants/ssoProviders'
import { USER_ACTIONS } from '@app/angular/constants/events'

import importIcAccountModal from './views/importIcAccountModal.html'
import importIcAccountSuccessModal from './views/importIcAccountSuccessModal.html'
import importIcAccountErrorModal from './views/importIcAccountErrorModal.html'

export default angular
  .module('ImportIcAccountServiceModule', [])
  // Service for performing a one-time import of your IC delivery addresses, payment information, retailer specific order history, and express membership
  .service(
    'importIcAccountService',
    function importIcAccountService(
      $rootScope,
      $state,
      $uibModal,
      analytics,
      auth,
      config,
      lang,
      localStorageService,
      redirectService,
      uibModalAriaLabelService
    ) {
      const service = this

      service.blockingStateTransition = ''
      service.loginContext = ''

      service.handleSocialSignOnSuccess = ({ code, redirectResponse }) => {
        const ignorePostImportModal = localStorageService.deprecatedGet('ignorePostImportModal')
        let linkSuccessful = false

        return authService.api.auth
          .linkAccount({ code, redirect_response: redirectResponse })
          .then(() => {
            if (ignorePostImportModal) {
              localStorageService.deprecatedSet(
                'postImportMessage',
                `${lang.values.icImportAccount.success.inProgress} ${lang.values.icImportAccount.success.pleaseWait}`
              )
            }
            linkSuccessful = true
            analytics.logSegmentEvent('user.ic_account_import_result', {
              success: true,
            })
          })
          .catch(() => {
            if (ignorePostImportModal) {
              localStorageService.deprecatedSet(
                'postImportError',
                lang.values.icImportAccount.error.tryAgain
              )
            }
            analytics.logSegmentEvent('user.ic_account_import_result', {
              success: false,
            })
          })
          .then(() => {
            if (!auth.user.context || !auth.user.context.intent) {
              auth.setContext({ intent: config.defaultShoppingContext })
            }
            return redirectService.loadRedirectUrl({ state: 'main.home' })
          })
          .then(() => {
            if (ignorePostImportModal) {
              localStorageService.deprecatedRemove('ignorePostImportModal')
            } else if (linkSuccessful) {
              return $uibModal.open({
                template: importIcAccountSuccessModal,
                controller: [
                  '$scope',
                  $scope => {
                    $scope.close = () => {
                      $scope.$close()
                    }
                  },
                ],
                backdrop: 'static',
                keyboard: true,
              }).closed
            } else {
              return $uibModal.open({
                template: importIcAccountErrorModal,
                controller: [
                  '$scope',
                  '$state',
                  ($scope, $state) => {
                    $scope.goToAccount = () => {
                      $state.go('main.account.profile')
                    }

                    $scope.close = () => {
                      $scope.$close()
                    }
                  },
                ],
                backdrop: 'static',
                keyboard: true,
              }).closed
            }
          })
      }

      service.shouldOpenModal = loginContext => {
        // Check if Instacart Import Account is enabled
        if (!config.icImportAccount) {
          return false
        }

        // Check if they have an IC User ID (they are isolated)
        if (!auth.user.ic_user_id) {
          return false
        }

        // Check if the user is a trial user or if they have opted out
        if (auth.user.status === 'trial' || auth.user.ic_import_opt_out) {
          return false
        }

        // Check to see if we are in a state which we do not want to
        // pop-up the modal
        if (config.icImportAccountModalBannedPopupRoutes?.some(route => $state.includes(route))) {
          return false
        }

        // Check if the sites config allow the popup, if it doesn't exist
        // in the config the return value will be falsey
        return config.icImportAccountModalEnablePopup[loginContext]
      }

      service.updateUserIcOptOut = () => {
        auth.updateProfile({ ic_import_opt_out: true })
      }

      // Modal service works uses promise chains, therefore if we should not
      // open the modal, we no-op by just returning a resolved promise
      service.openModal = loginContext =>
        service.shouldOpenModal(loginContext) ? service.open() : Promise.resolve()

      service.open = () =>
        $uibModal.open({
          template: importIcAccountModal,
          controller: [
            '$scope',
            'socialSignOnService',
            ($scope, socialSignOnService) => {
              $scope.cancel = params => {
                if (params.skippedButtonClicked) {
                  analytics.logSegmentEvent('user.skip_ic_account_import', {
                    ic_account_opt_out: params.importOptOut,
                    modal_dismissed: false,
                  })
                  if (params.importOptOut) {
                    service.updateUserIcOptOut()
                  }
                } else {
                  analytics.logSegmentEvent('user.skip_ic_account_import', {
                    modal_dismissed: true,
                  })
                }
                $scope.$close()
              }

              $scope.onImportClick = params => {
                if (params.importOptOut) {
                  service.updateUserIcOptOut()
                }
                analytics.logSegmentEvent('user.trigger_ic_account_import', {
                  source: 'post-registration',
                  ic_account_opt_out: params.importOptOut,
                })
                socialSignOnService.handleSocialSignOn(SSO_PROVIDERS.INSTACART_LINK)
              }
            },
          ],
          backdrop: 'static',
          keyboard: true,
          resolve: {
            ariaLabel: () => {
              uibModalAriaLabelService.setLabel(lang.values.icImportAccount.title)
            },
          },
        }).result

      $rootScope.$on('auth', (_event, action, loginContext) => {
        switch (action) {
          case USER_ACTIONS.POST_USER_LOGGED_IN_STATE_RELOAD:
            return service.openModal(loginContext)
          case USER_ACTIONS.POST_USER_LOGGED_IN_NO_STATE_RELOAD:
            return $state.includes('main.checkout') && service.openModal(loginContext)
          case USER_ACTIONS.POST_USER_EXTERNAL_LOGIN:
            if ($state.includes('main.checkout.cartV2') && auth.getContext().intent === 'instore') {
              service.blockingStateTransition = 'main.inStoreList.review'
              service.loginContext = loginContext
            } else {
              return service.openModal(loginContext)
            }
        }
      })

      $rootScope.$on(
        '$stateChangeSuccess',
        (_event, state, _params, _previousState, _previousParams) => {
          if (service.blockingStateTransition !== '') {
            // eslint-disable-next-line eqeqeq
            if (state.name == service.blockingStateTransition) {
              service.blockingStateTransition = ''
              service.openModal(service.loginContext)
              service.loginContext = ''
            }
          }
        }
      )
    }
  )
