import angular from 'angular'
import {
  MESSAGE_PLACEMENTS,
  EXPRESS_SUBSCRIPTION_PLAN_TERMS,
  HTML_TEMPLATES,
} from '@app/angular/constants/express'
import template from './icExpressCancellation.html'

const CANCEL_STEPS = {
  FIRST_STEP: 1,
  SECOND_STEP: 2,
  THIRD_STEP: 3,
  FORTH_STEP: 4,
}

class IcExpressCancellation {
  constructor($scope, $state, config, express, lang) {
    Object.assign(this, {
      $scope,
      $state,
      config,
      express,
      lang,
      currentStep: CANCEL_STEPS.FIRST_STEP,
      useIcTheme: !config.icExpress.enableRetailerTheme,
      CANCEL_STEPS,
    })
  }

  $onInit() {
    this.termsOfService = this._getTermsOfService()
  }

  _getTermsOfService() {
    const { icExpress } = this.config
    const { url, altLabel } = icExpress.termsAndConditions

    return HTML_TEMPLATES.TERMS(url, altLabel)
  }

  async nextStep() {
    // If the user is already enrolled in a monthly plan, we show the form step
    if (
      this.currentStep === CANCEL_STEPS.FIRST_STEP &&
      this.express.isTrial() &&
      this.express.getNextPlan().id ===
        (await this.express.getPlanIds(EXPRESS_SUBSCRIPTION_PLAN_TERMS.MONTHLY))
    ) {
      this.currentStep = CANCEL_STEPS.THIRD_STEP
      return
    }

    this.currentStep += 1
    this.$scope.$apply()
  }

  exit() {
    this.$state.go('main.account.icExpress')
  }

  turnOnReminders() {
    const subscriptionId = this.express.userCurrentSubscription?.subscription?.id

    this.express
      .updateSubscription({
        subscription_id: subscriptionId,
        send_reminder_email: true,
      })
      .then(() => {
        this.express.setMessage(
          this.lang.values.reminderUpdated,
          this.lang.values.reminderTurnedOn,
          null,
          MESSAGE_PLACEMENTS.PAYMENT
        )
        this.exit()
      })
  }

  async updateMembership() {
    return this.express
      .getPlanIds(EXPRESS_SUBSCRIPTION_PLAN_TERMS.MONTHLY)
      .then(planId => this.express.updateMembershipPlan(planId))
      .then(() => {
        this.express.setMessage(
          this.config.icExpress.membershipUpdate,
          this.lang.values.icExpress.membershipPlanUpdate
        )
        this.exit()
        this.$scope.$apply()
      })
  }

  onMembershipCancel({ option, details }) {
    const subscriptionId = this.express.userCurrentSubscription?.subscription?.id

    return this.express
      .updateSubscription({
        subscription_id: subscriptionId,
        cancel: true,
        reason: option,
        additional_notes: details,
      })
      .then(() => this._handleCancellationSuccess())
  }

  _handleCancellationSuccess() {
    const { showNewProfilePage } = this.config.icExpress.profilePage
    !showNewProfilePage &&
      this.express.setMessage(
        this.config.icExpress.membershipUpdate,
        this.config.icExpress.cancellation.trialCancelMessage
      )
    this.nextStep()
  }
}

export default angular
  .module('IcExpressCancellationComponentModule', [])
  .component('icExpressCancellation', {
    template,
    controller: IcExpressCancellation,
    controllerAs: '$ctrl',
  })
