import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { SHOPPING_SELECTOR_STAGES } from '@app/angular/constants/shoppingSelectorStages'

import { hasRxItem } from '@instacart/enterprise-services/shopping-list/_common/utils'

export default angular
  .module('userOverridesServicesModule', [])
  .service(
    'userOverridesService',
    function userOverridesService(
      $q,
      $rootScope,
      Restangular,
      auth,
      cartService,
      catalogCategoriesService,
      compareCartService,
      config,
      eComLightService,
      shoppingContextService,
      shoppingSelectorService,
      unataCommService
    ) {
      const service = this

      // User can be forced into a certain store/context through URL "override" params or a config.
      // This service is used to handle these overrides.

      // service.hideCartCompareBeforeOverride
      // service.openToStoreSelectorStep
      // service.userOverrideStateParams
      service.listOfOverrideParams = [
        'b2b_store',
        'delivery_zip',
        'hide_cart_compare_on_arrival',
        'shopping_context',
        'store',
        'store_id',
        'confirm_context',
        'enter_location',
      ]

      const overrideData = {}

      service.getIntent = context =>
        context === SHOPPING_MODES.CATERING ? SHOPPING_MODES.PICKUP : context

      service.getContext = intent => ({ intent: service.getIntent(intent), shopping_mode: intent })

      service.parseOverrideParams = stateParams => {
        // Parse config and URL params provided into user override data

        service.userOverrideStateParams = fp.flow(
          fp.pick(service.listOfOverrideParams),
          fp.omitBy(_.isNil)
        )(stateParams)

        service.hideCartCompareBeforeOverride = stateParams.hide_cart_compare_on_arrival

        // If true, open the contextSelector modal with the context highlighted.
        // Note. if confirmContext and enterLocation are both true, confirmContext takes precedence.
        service.confirmContext = stateParams.confirm_context === 'true'

        if (service.confirmContext) {
          unataCommService.send({
            eventType: unataCommService.EventType.CONFIRMCONTEXT,
            payload: undefined,
          })
        }

        // If true, this will force the storeSelector to show
        service.enterLocation = stateParams.enter_location === 'true'
        // User context overrides
        overrideData.newContext = service.userOverrideStateParams.shopping_context

        // User store overrides - they have the following priority order:

        // External Store override
        // User will be placed into a store with the provided external store id (ONLY IF A NEW TRIAL USER IS BEING CREATED)
        overrideData.newExternalStoreId = service.userOverrideStateParams.store

        // Store override
        const forcedStoreId = config.forceStoreId
        if (forcedStoreId) {
          overrideData.newStoreId = forcedStoreId
        } else if (service.userOverrideStateParams.b2b_store) {
          overrideData.newStoreId = service.userOverrideStateParams.b2b_store
        } else if (service.userOverrideStateParams.store_id) {
          overrideData.newStoreId = service.userOverrideStateParams.store_id
        }

        // Zip override
        overrideData.newZip = service.userOverrideStateParams.delivery_zip
      }

      const generateSuccessfulOverridePayload = ({
        newContext,
        newStoreId,
        newZip,
        currentContext,
        currentStoreId,
        currentZip,
        currentShoppingMode,
      }) => {
        const overridePayload = {}
        const addContextToPayload =
          (newContext === SHOPPING_MODES.CATERING && newContext !== currentContext) ||
          newContext !== currentShoppingMode

        const addStoreIdToPayload = newStoreId && newStoreId !== currentStoreId
        const addZipToPayload = newZip && newZip !== currentZip

        if (addContextToPayload || addZipToPayload) {
          overridePayload.context = {}

          if (addContextToPayload) {
            overridePayload.context = service.getContext(newContext)
          }

          if (addZipToPayload) {
            overridePayload.context.deliveryPostalCode = newZip
          }
        }

        if (addStoreIdToPayload) {
          overridePayload.store_id = newStoreId
        }

        return overridePayload
      }

      // overridePayload is what is needed while creating or updating the user to set the overrides
      // shoppingSelectorStep is the unified shopping selector screen to redirect the user to (if necessary) after the auth call
      // eslint-disable-next-line complexity
      const determineOverridePayloadAndShoppingSelectorStep = (
        { newContext, newStoreId, newZip },
        currentUser
      ) => {
        const payloadAndSelectorStep = $q.defer()

        let currentContext
        let currentStoreId
        let currentZip
        let currentShoppingMode

        if (currentUser) {
          if (currentUser.store) {
            currentStoreId = currentUser.store.id
          }

          if (currentUser.context) {
            currentContext = currentUser.context.intent
            currentZip =
              currentUser.context.deliveryPostalCode ?? currentUser.store.address.postal_code
            currentZip = currentZip.includes('-') ? currentZip.substring(0, 5) : currentZip
            currentShoppingMode = currentUser.context.shopping_mode
          }
        }

        const shouldUpdateContext = context => {
          if (!context) return false

          if (
            context === SHOPPING_MODES.CATERING &&
            currentShoppingMode !== SHOPPING_MODES.CATERING
          ) {
            return true
          }

          if (context !== currentShoppingMode) {
            return true
          }

          return false
        }

        // If the shopping_context override was passed as e.g. catering, but catering isn't enabled,
        // override the parameter with the mode's behaveAs, if any. This allows us to support fourth
        // mode catering alongside the legacy "pickup as catering" mode without requiring retailers
        // to alter the links in their email engagement, etc.
        if (
          !config.shoppingSelector?.[newContext]?.exists &&
          config.shoppingSelector?.[newContext]?.behaveAs
        ) {
          newContext = config.shoppingSelector[newContext].behaveAs
        }

        if (
          // Don't allow zip override if no delivery override is specified
          (newZip && newContext !== SHOPPING_MODES.DELIVERY) ||
          (newContext &&
            // Don't allow invalid context override
            (!Object.values(SHOPPING_MODES).includes(newContext) ||
              // Don't allow context overrides for shopping contexts that are not enabled
              !(config.shoppingSelector[newContext] && config.shoppingSelector[newContext].exists)))
        ) {
          payloadAndSelectorStep.reject()
        } else if (
          shouldUpdateContext(newContext) ||
          (newStoreId && newStoreId !== currentStoreId) ||
          (newZip && newZip !== currentZip)
        ) {
          const destinationContext = newContext || currentContext
          const destinationStoreId = newStoreId || currentStoreId
          const destinationZip =
            destinationContext === SHOPPING_MODES.DELIVERY ? newZip || currentZip : null

          // We want to make sure that the mix of existing (if any) context/store/zip and new context/store/zip is valid
          //  (e.g. that the resulting user store supports the resulting shopping context, etc).
          // If the combination is supported, we provide the required user call payload to make that combination happen.
          // If this is combination is not supported, we provide the user call payload for the overrides that can be performed (if any)
          //  and which Unified Shopping Selector step to then place the user into so they can put themselves in a valid state
          //  given the desired override parameters specified in the URL.

          // If there are no issues with the overrides, this will be the payload
          const successfulOverridePayload = generateSuccessfulOverridePayload({
            newContext,
            newStoreId,
            newZip,
            currentContext,
            currentStoreId,
            currentZip,
            currentShoppingMode,
          })

          if (destinationStoreId && !destinationZip) {
            // If the user has a destination store and zipcodes are not part of the equation,
            //  fetch that specific store and make sure it supports the user's destination context
            Restangular.one('stores', destinationStoreId)
              .get()
              .then(store => {
                // Store exists

                if (
                  (destinationContext === SHOPPING_MODES.PICKUP && !store.show_pickup) ||
                  (destinationContext === SHOPPING_MODES.DELIVERY && !store.show_delivery) ||
                  (destinationContext === SHOPPING_MODES.CATERING && !store.show_catering)
                ) {
                  // If the store does NOT support the context the user will end up in

                  if (!newContext && newStoreId) {
                    // If only store id override was given, and that store exists, process override but make the user changes context
                    // ("Clean" override failed because user's old context did not support store override)
                    payloadAndSelectorStep.resolve({
                      overridePayload: { context: { intent: null }, store_id: newStoreId },
                      shoppingSelectorStep: 'shoppingContext',
                    })
                  } else if (!newStoreId && newContext) {
                    // If only context override was given, and that context exists, process override but make the user change stores
                    // ("Clean" override failed because user's old store did not support context override)
                    payloadAndSelectorStep.resolve({
                      overridePayload: {
                        context: service.getContext(newContext),
                        store_id: null,
                      },
                      shoppingSelectorStep: 'storeSelector',
                    })
                  } else {
                    // Otherwise, no overrides are possible
                    payloadAndSelectorStep.reject()
                  }
                } else {
                  // If the store supports the context the user will end up in (or if no contexts are set),
                  //  process override for context, store, or both

                  let shoppingSelectorStep
                  // Send the user to the zip input in the Unified Shopping Selector if they end up in delivery (since they'll need a zipcode)
                  if (newContext === SHOPPING_MODES.DELIVERY) {
                    shoppingSelectorStep = 'storeSelector'
                  }

                  payloadAndSelectorStep.resolve({
                    overridePayload: successfulOverridePayload,
                    shoppingSelectorStep,
                  })
                }
              })
              .catch(() => {
                // Store does not exist - overrides are not possible
                payloadAndSelectorStep.reject()
              })
          } else {
            // Otherwise, fetch all stores filtered by the destination zipcode and context,
            // and make sure it supports the user's destination store
            const storesRequestPayload = {}

            if (destinationContext === SHOPPING_MODES.PICKUP) {
              storesRequestPayload.show_pickup = true
            } else if (destinationContext === SHOPPING_MODES.DELIVERY) {
              storesRequestPayload.show_delivery = true

              if (destinationZip) {
                storesRequestPayload.delivery_postal_code = destinationZip
              }
            } else if (destinationContext === SHOPPING_MODES.CATERING) {
              storesRequestPayload.show_catering = true
            }

            Restangular.all('stores')
              .getList(storesRequestPayload)
              .then(stores => {
                let supportedStores = stores

                if (destinationStoreId) {
                  supportedStores = _.filter(supportedStores, { id: destinationStoreId })
                }

                if (supportedStores.length > 0) {
                  // Found a store for the user's destination state, process overrides
                  const overridePayloadToReturn = { ...successfulOverridePayload }

                  if (!destinationStoreId && newContext === SHOPPING_MODES.DELIVERY && newZip) {
                    // If a delivery context and zipcode override has been provided without a store assigned to the user, or
                    //  without a store override, place the user in the first available store that supports delivery in the provided zip
                    overridePayloadToReturn.store_id = supportedStores[0].id
                  }
                  payloadAndSelectorStep.resolve({ overridePayload: overridePayloadToReturn })
                } else {
                  // If not, deal with some edge cases to try and process some overrides and send user to a Unified Shopping Selector screen
                  // (We only want to process overrides if they are all potentially valid - so if a retailer tries the override the user with a
                  //  store and context, and the store does not support that context, we don't try and do one of the overrides, we do neither.)

                  // eslint-disable-next-line no-lonely-if
                  if (newContext) {
                    if (newContext !== SHOPPING_MODES.DELIVERY || (!newStoreId && !newZip)) {
                      // If only context override was given, and that context exists, process override but make the user change stores
                      // ("Clean" override failed because user's old store did not support context override)
                      payloadAndSelectorStep.resolve({
                        overridePayload: {
                          context: service.getContext(newContext),
                          store_id: null,
                        },
                        shoppingSelectorStep: 'storeSelector',
                      })
                    } else {
                      // if (newContext === 'delivery' && (newStoreId || newZip))

                      // eslint-disable-next-line no-lonely-if
                      if (newStoreId && newZip) {
                        // If all 3 overrides (with newContext === 'delivery) were provided - reject
                        payloadAndSelectorStep.reject()
                      } else if (newStoreId && currentZip) {
                        // only newContext === 'delivery' & newStoreId overrides were provided

                        // see if the override store supports delivery (i.e. try again without user's old zip) - otherwise, reject
                        const fakeCurrentUser = {
                          context: { intent: currentContext },
                          store: { id: currentStoreId },
                        }
                        determineOverridePayloadAndShoppingSelectorStep(
                          { newContext, newStoreId, newZip },
                          fakeCurrentUser
                        )
                          .then(() => {
                            // If this resolves, then we've proven our theory that the user's old zip was preventing the overrides
                            // Clear the user's zip and send them to the zip input in the Unified Shopping Selector after the override
                            payloadAndSelectorStep.resolve({
                              // Return whatever the override payload would've been but with the zipcode as `null` to clear it
                              overridePayload: {
                                ...successfulOverridePayload,
                                context: {
                                  ...successfulOverridePayload.context,
                                  deliveryPostalCode: null,
                                },
                              },
                              shoppingSelectorStep: 'storeSelector',
                            })
                          })
                          .catch(() => {
                            payloadAndSelectorStep.reject()
                          })
                      } else if (newZip && currentStoreId) {
                        // only newContext === 'delivery' & newZip overrides were provided

                        // see if there are any stores that support delivery with the override zip (i.e. try again without user's old store) - otherwise, reject
                        const fakeCurrentUser = {
                          context: { intent: currentContext, deliveryPostalCode: currentZip },
                        }
                        determineOverridePayloadAndShoppingSelectorStep(
                          { newContext, newStoreId, newZip },
                          fakeCurrentUser
                        )
                          .then(({ overridePayload, shoppingSelectorStep }) => {
                            // If this resolves cleanly, then we've proven our theory that the user's old store was preventing the overrides
                            // The override generated from this call will provide us with the first available store that supports "delivery" for newZip
                            // We should redirect the user to this store for this case (where the old user store was preventing the overrides) as well

                            // Clear the user's store and send them to the store selector in the Unified Shopping Selector after the override
                            if (!shoppingSelectorStep) {
                              payloadAndSelectorStep.resolve({
                                // Return whatever the override payload would've been but including the first available store that supports newZip
                                overridePayload: {
                                  ...successfulOverridePayload,
                                  store_id: overridePayload.store_id,
                                },
                              })
                            } else {
                              payloadAndSelectorStep.reject()
                            }
                          })
                          .catch(() => {
                            payloadAndSelectorStep.reject()
                          })
                      } else {
                        // There is no currentStoreId or currentZip that could've gotten in the way of the override - reject
                        payloadAndSelectorStep.reject()
                      }
                    }
                  } else {
                    // if (newStoreId && !newContext && !newZip && currentContext === 'delivery')

                    // see if the override store exists (i.e. try again with empty user) - otherwise reject
                    determineOverridePayloadAndShoppingSelectorStep(
                      { newContext, newStoreId, newZip },
                      {}
                    )
                      .then(({ shoppingSelectorStep }) => {
                        // If this resolves cleanly (unless no existing user zip exists), then we've proven our theory that the user's
                        //  old delivery context or zip was preventing the override
                        // Since the user is unlikely to want to change their zipcode, we should make the user change context
                        if (!currentZip || (currentZip && !shoppingSelectorStep)) {
                          payloadAndSelectorStep.resolve({
                            // Return whatever the override payload would've been but with user context as `null` to clear it
                            overridePayload: {
                              ...successfulOverridePayload,
                              context: { ...successfulOverridePayload.context, intent: null },
                            },
                            shoppingSelectorStep: 'shoppingContext',
                          })
                        } else {
                          payloadAndSelectorStep.reject()
                        }
                      })
                      .catch(() => {
                        payloadAndSelectorStep.reject()
                      })
                  }
                }
              })
              .catch(() => {
                // Call to `/stores` failed for some reason, no overrides are possible
                payloadAndSelectorStep.reject()
              })
          }
        } else {
          // No overrides needed
          payloadAndSelectorStep.resolve({ overridePayload: {} })
        }

        return payloadAndSelectorStep.promise.then(({ overridePayload, shoppingSelectorStep }) => {
          // Any existing user zip must be included in the payload's context since PATCHes to /user
          //  will clear the existing deliveryPostalCode if not included with it
          if (
            currentZip &&
            overridePayload.context &&
            !overridePayload.context.deliveryPostalCode &&
            overridePayload.context.deliveryPostalCode !== null
          ) {
            overridePayload.context.deliveryPostalCode = currentZip
          }
          return { overridePayload, shoppingSelectorStep }
        })
      }
      // For Unit Test
      service._determineOverridePayloadAndShoppingSelectorStep_ = determineOverridePayloadAndShoppingSelectorStep

      // This can be called to get the required API call payload to set the user in those desired "overrides",
      service.getUserOverridePayload = () => {
        // If currentUser is undefined, it means we are creating a new trial user

        const userOverridePayload = $q.defer()

        const currentUser = auth.user

        if (!currentUser && overrideData.newExternalStoreId) {
          userOverridePayload.resolve({ ext_store_id: overrideData.newExternalStoreId })
        } else {
          determineOverridePayloadAndShoppingSelectorStep(overrideData, currentUser)
            .then(({ overridePayload, shoppingSelectorStep }) => {
              // If either confirm_context or enter_location is present, these take precendence over the
              // modals set by shoppingSelectorStep below. Therefore, only set the openToStoreSelectorStep if both
              // deeplinks are not present
              if (!service.confirmContext && !service.enterLocation) {
                service.openToStoreSelectorStep = shoppingSelectorStep
              } else {
                service.openToStoreSelectorStep = null
              }

              userOverridePayload.resolve(overridePayload)
            })
            .catch(() => {
              // Overrides are not possible
              console.error('None of the user overrides provided in URL could be performed.')
              userOverridePayload.resolve({})
            })
        }

        return userOverridePayload.promise
      }

      // Sometimes after a user override we need to open the store selector
      //  because we couldn't place them in all of the provided overrides
      service.openShoppingSelectorAfterOverride = () => {
        shoppingSelectorService.open(service.openToStoreSelectorStep)
        service.openToStoreSelectorStep = null
      }

      service.handleContextModalDeepLinks = () => {
        if (
          // Exit if none of the parameters are present
          (!service.confirmContext && !service.enterLocation) ||
          // Exit if trying to enter pickup with rx items
          (overrideData.newContext === SHOPPING_MODES.PICKUP && hasRxItem(cartService?.cart?.items))
        ) {
          return
        }

        shoppingContextService.setSelectedFulfillmentType(overrideData.newContext)

        if (overrideData.newContext === SHOPPING_MODES.CATERING) {
          eComLightService.setMode(true)
        }
        if (service.confirmContext) {
          shoppingSelectorService.open(SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT)
        } else if (service.enterLocation) {
          shoppingSelectorService.open(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
        }
        service.confirmContext = null
        service.enterLocation = null
      }

      service.updateExistingUserWithOverrides = () => {
        const deferred = $q.defer()
        service.getUserOverridePayload().then(userOverridePayload => {
          if (!_.isEmpty(userOverridePayload)) {
            // If there are any user overrides to perform on the existing user

            const cartComparePayload = _.omitBy(
              {
                store_id: userOverridePayload.store_id,
                fulfillment_type:
                  (userOverridePayload.context && userOverridePayload.context.intent) || null,
              },
              _.isNil
            )

            const updateUserWithOverrides = () =>
              auth
                .update(userOverridePayload)
                .then($rootScope.reloadState)
                // Available categories may change, so refresh them.
                .then(() => catalogCategoriesService.fetchCategories(true))
                .then(deferred.resolve)
                .catch(error => {
                  console.error('Error updating user with overrides provided in URL:', error)
                  deferred.reject()
                })

            if (service.hideCartCompareBeforeOverride || _.isEmpty(cartComparePayload)) {
              // Skip the cart compare and go ahead with the user update
              updateUserWithOverrides()
            } else {
              // Call cart compare and if the user agrees, continue with the user update; otherwise don't update user with overrides
              // LAWRENCE TODO: Check to see if this cart call is required
              cartService
                .fetch()
                .then(() => compareCartService.compareCart(true, 'store', cartComparePayload))
                .then(updateUserWithOverrides)
            }
          } else if (userOverridesService.openToStoreSelectorStep) {
            // If no overrides need to be performed, check if we've indicated that the shopping selector should be opened
            service.openShoppingSelectorAfterOverride()
            deferred.resolve()
          } else {
            deferred.resolve()
          }
        })
        return deferred.promise
      }
    }
  )
