import isCapacitor from '@app/utils/isCapacitor'
import UrbanAirshipPlugin from '@app/react/mobile/plugins/urbanAirshipPlugin'

export function getUrbanAirshipChannelId(cb: (id: string) => void) {
  if (isCapacitor()) {
    UrbanAirshipPlugin.getChannel().then(channel => cb(channel.id))
  }
  if (window.UAirship) {
    window.UAirship.getChannelID(channelID => cb(channelID))
  }
}
