import angular from 'angular'
import throttle from 'lodash/throttle'
import ResizeObserver from 'resize-observer-polyfill'

export default angular.module('UnataCarouselModule', []).component('unataCarousel', {
  restrict: 'E',
  transclude: true,
  bindings: {
    name: '<',
  },

  controller($element, $scope, $timeout, $window, lang, mobileService) {
    this.lang = lang

    const container = $element[0]
    const content = container.querySelector('ng-transclude')

    const parseScrollLeft = () =>
      -parseInt(content.style.transform.replace(/^translateX\((.+)\)/, '$1'), 10) || 0

    const queryCells = () => [...content.querySelectorAll('.cell-wrapper')]

    const queryFocusableElements = element =>
      element.querySelectorAll(
        'a, button, input, textarea, select, details, [tabindex]:not([tabindex="-1"])'
      )

    const updateCells = (direction, focusFirst) => {
      const cells = queryCells()
      if (!cells.length) {
        return
      }

      const minOffset = direction !== undefined && direction < 0 ? -container.clientWidth : 0
      const maxOffset = (direction !== undefined && direction > 0 ? 2 : 1) * container.clientWidth
      const scrollLeft = parseScrollLeft()
      let firstCell
      cells.forEach(cell => {
        const cellOffset = cell.offsetLeft - scrollLeft
        const hidden = cellOffset + cell.offsetWidth < minOffset || cellOffset > maxOffset
        const wasHidden = cell.getAttribute('aria-hidden') === 'true'

        // Hide cells outside of the carousel viewport for a11y.
        if (hidden !== wasHidden) {
          cell.setAttribute('aria-hidden', hidden)
          cell.style.visibility = hidden ? 'hidden' : ''
        }

        if (direction !== undefined) {
          return
        }

        const partiallyHidden =
          (cellOffset < minOffset || cellOffset + cell.offsetWidth > maxOffset) && !hidden
        if (partiallyHidden || cell.dataset.partiallyHidden === 'true') {
          if (partiallyHidden) {
            cell.dataset.partiallyHidden = true
          } else {
            delete cell.dataset.partiallyHidden
          }

          // Make partially visible cells unfocusable for a11y.
          const elements = queryFocusableElements(cell)
          ;[...elements].forEach(element => {
            if (partiallyHidden) {
              element.dataset.tabIndex = element.tabIndex
              element.tabIndex = -1
            } else {
              element.tabIndex = Number(element.dataset.tabIndex)
              delete element.dataset.tabIndex
            }
          })
        }

        if (!hidden && !partiallyHidden) {
          firstCell ??= cell
        }
      })

      if (focusFirst && firstCell) {
        // Focus the first visible cell after scrolling for a11y.
        const elements = queryFocusableElements(firstCell)
        elements[0]?.focus()
      }
    }

    const updateButtons = () => {
      const scrollLeft = parseScrollLeft()
      $scope.$applyAsync(() => {
        this.showPrevious = scrollLeft > 0
        this.showNext = scrollLeft < content.offsetWidth - container.clientWidth
      })
    }

    const update = (...args) => {
      updateCells(...args)
      updateButtons()
    }

    let slideTimeout
    this.slide = direction => {
      const cells = queryCells()

      const scrollLeft = parseScrollLeft()
      let offset
      if (cells.length) {
        if (direction < 0) {
          cells.reverse()
        }
        let cellOffset
        cells.forEach(cell => {
          if (offset !== undefined) {
            return
          }
          cellOffset = (cell.offsetLeft - scrollLeft) * direction
          if (cellOffset + cell.offsetWidth > container.clientWidth) {
            offset = cellOffset
          }
        })
        offset ??= cellOffset
      } else {
        offset = container.clientWidth
      }

      updateCells(direction)

      slideTimeout = $timeout(() => {
        $timeout.cancel(slideTimeout)

        update(undefined, direction > 0)
      }, 300)

      let translateX = scrollLeft + offset * direction
      translateX = Math.max(translateX, 0)
      translateX = Math.min(translateX, content.offsetWidth - container.clientWidth)
      content.style.transform = `translateX(${-translateX}px)`
    }

    if (!mobileService.isMobile) {
      const mutationObserver = new MutationObserver(mutations => {
        if (
          !mutations.some(({ addedNodes }) =>
            [...addedNodes].some(({ nodeType }) => nodeType === Node.ELEMENT_NODE)
          )
        ) {
          return
        }

        mutationObserver.disconnect()
        update()
      })
      mutationObserver.observe(content, { childList: true })

      const resizeObserver = new ResizeObserver(() => {
        update()
      })
      resizeObserver.observe(content)

      const onResize = throttle(() => {
        update()
      }, 1000)
      $window.addEventListener('resize', onResize)
      $scope.$on('$destroy', () => {
        $window.removeEventListener('resize', onResize)
      })
    }
  },

  template: `
    <section aria-label="{{::$ctrl.name}} {{::$ctrl.lang.aria.carousel}}">
      <p class="screen-reader-text">{{::$ctrl.lang.aria.carouselInstructions}}</p>
      <button
        class="carousel-button carousel-button-previous icon-chevron-left"
        ng-class="{'carousel-hidden': !$ctrl.showPrevious}"
        arial-label="{{::$ctrl.lang.aria.previous}}"
        title="{{::$ctrl.lang.aria.previous}}"
        ng-click="$ctrl.slide(-1)"
      ></button>
      </span>
      <ng-transclude></ng-transclude>
      <span
        class="carousel-overlay"
        ng-class="{'carousel-hidden': !$ctrl.showNext}"
      ></span>
      <button
        class="carousel-button carousel-button-next icon-chevron-right"
        ng-class="{'carousel-hidden': !$ctrl.showNext}"
        arial-label="{{::$ctrl.lang.values.next}}"
        title="{{::$ctrl.lang.values.next}}"
        ng-click="$ctrl.slide(1)"
      ></button>
    </section>
  `,
})
