import { QUEBEC_POSTAL_DISTRICT_LETTERS } from '@app/angular/constants/address'

/**
 * Returns version of the stores list that puts the currently active store at
 * the top of the list
 *
 * @param {number|string} storeID
 * @param {Store[]|null} stores The list of stores (or null)
 * @returns
 */
export function prioritizeActiveStore(storeID, stores) {
  // Exit early to account for stores being null
  if (!stores || storeID === undefined || storeID === null) {
    return stores
  }

  const strStoreID = `${storeID}`
  const activeStoreIdx = stores.findIndex(store => {
    return `${store.id}` === strStoreID
  })

  const shouldPreserveWhenNotInTheList = activeStoreIdx < 0
  if (shouldPreserveWhenNotInTheList || activeStoreIdx === 0) {
    return stores
  }

  // Construct a new array with the active store at the front and the rest of
  // the array preserving the same order
  return [
    stores[activeStoreIdx],
    ...stores.slice(0, activeStoreIdx),
    ...stores.slice(activeStoreIdx + 1),
  ]
}

/**
 * Checks if the given postal code is unsupported based on certain criteria.
 * Currently, this 'criteria' is used to check for Quebec postal district codes, for Costco Canada.
 *
 * @param {string} postalCode
 */
export function isUnsupportedPostalCode(postalCode = '') {
  return QUEBEC_POSTAL_DISTRICT_LETTERS.includes(postalCode[0])
}
