/* eslint-disable max-lines */

import _ from 'lodash'
import angular from 'angular'
import { CHECKOUT_ROUTES_FACTORY } from '@app/angular/constants/checkout'
import { order } from '@instacart/enterprise-services'
import { OneCartActions } from '@app/services/oneCart/types'

let newCheckoutExitTimer = null

const oneCartCheckoutRedirectFunc = [
  '$rootScope',
  '$state',
  '$timeout',
  'auth',
  'config',
  'oneCartService',
  ($rootScope, $state, $timeout, auth, config, oneCartService) => {
    $timeout(() => {
      /**
       * When OneCart is enabled, and the user is trying to access the v2 or v1 cart review page,
       * we redirect them back to the main page with the sidecart open
       */
      if (oneCartService.enabled()) {
        const isDeepLinking = typeof $rootScope.previousState === 'undefined'
        const params = isDeepLinking ? { open_sidecart: true } : null

        $state.go('main.home', params, {
          reload: true,
        })

        if (!isDeepLinking) {
          $rootScope.$broadcast('sideCart', 'open')
        }
      }
    })
  },
]

export default angular
  .module('checkoutRoutesServiceModule', [])
  .service('checkoutRoutes', function ($state, configConstants, lang, runtimeRoutes) {
    const newCheckoutConfig = configConstants.newCheckoutFlow
    const CHECKOUT_ROUTES = CHECKOUT_ROUTES_FACTORY(newCheckoutConfig)
    const service = this

    service.init = () => {
      // Main
      runtimeRoutes.addState('main.checkout', {
        url: '/checkout',
        data: {},
        views: {
          'content@': {
            template: '<div ui-view="checkout" class="checkout view"></div>',
            controller: [
              '$scope',
              'progressSegments',
              function ($scope, progressSegments) {
                $scope.showCartTotals = true
                $scope.loadCartUpsell = true // Make sure we only load upsell products in the cart view, not the side cart
                $scope.progressSegments = progressSegments
              },
            ],
          },
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          upsellProducts: [
            'manualUpsellService',
            manualUpsellService =>
              configConstants.enableCheckoutManualUpsell ||
              configConstants.newCheckoutFlow?.upsellPage?.enabled
                ? manualUpsellService.getUpsellProducts()
                : [],
          ],
          progressSegments: [
            'lang',
            'upsellProducts',
            (lang, upsellProducts) => {
              const segments = [
                {
                  id: 'checkout-cart',
                  label: lang.values.checkoutProgress.reviewCart,
                  sref: 'main.checkout.cart',
                },
              ]

              if (upsellProducts.length > 0) {
                segments.push({
                  id: 'checkout-manual-upsell',
                  label: lang.values.checkoutProgress.manualUpsell,
                  sref: 'main.checkout.manualUpsell',
                })
              }

              segments.push(
                {
                  id: 'checkout-reservation',
                  label: lang.values.checkoutProgress.reservation,
                  sref: 'main.checkout.reservation.confirmation',
                },
                {
                  id: 'checkout-payment',
                  label: lang.values.checkoutProgress.payment,
                }, // This page cannot be navigated to in progress indicator
                {
                  id: 'checkout-confirmation',
                  label: lang.values.checkoutProgress.confirmation,
                } // This page cannot be navigated to in progress indicator
              )

              return segments
            },
          ],

          cart: [
            'cartService',
            function (cartService) {
              if (!cartService.syncing) {
                return cartService.fetch()
              }
            },
          ],
        },
        onEnter: [
          '$rootScope',
          '$state',
          '$timeout',
          'auth',
          'cartService',
          'config',
          'checkoutAnalytics',
          'checkoutService',
          'oneCartCheckoutRedirect',
          'oneCartService',
          'utilService',
          'unataCommService',
          (
            $rootScope,
            $state,
            $timeout,
            auth,
            cartService,
            config,
            checkoutAnalytics,
            checkoutService,
            oneCartCheckoutRedirect,
            oneCartService,
            utilService,
            unataCommService
          ) => {
            const redirect = oneCartCheckoutRedirect
            if (redirect?.location) {
              oneCartService.redirectToPBI(redirect.location, redirect.auth_token, {
                action: OneCartActions.CHECKOUT,
              })
            }

            // If a user goes into a checkout state in instore mode, send them to list review.
            if (auth.getContext().intent === 'instore') {
              $timeout(() => {
                const lastState = utilService.getLastState()
                const lastStateName = lastState && lastState.name
                $state.go(
                  !lastStateName || !lastStateName.match('main.inStoreList')
                    ? 'main.inStoreList.review'
                    : 'main.home'
                )
              })
            }

            $timeout.cancel(newCheckoutExitTimer)

            // Force clear one-time unata comm cached listeners so that when
            // later returning to the shop and loading the dotcom header, the
            // one-time events fire again
            unataCommService.clearGlobalCachedListeners()
          },
        ],
        onExit: [
          '$timeout',
          'checkoutAnalytics',
          ($timeout, checkoutAnalytics) => {
            // We use a delay for exit because if there's a quick exit and enter (e.g. a popover), we don't want to exit!
            newCheckoutExitTimer = $timeout(() => {
              checkoutAnalytics.isInNewCheckoutFlow = false
              checkoutAnalytics.exitCheckoutFlow()
            })
          },
        ],
      })

      // Cart
      runtimeRoutes.addState('main.checkout.cart', {
        url: CHECKOUT_ROUTES.V1.CART,
        views: {
          checkout: {
            template: require('@app/angular/components/checkout/views/checkoutCart.html'),
            controller: 'CheckoutCartController',
          },
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          title: ['titleService', titleService => titleService.setTitle(lang.values.reviewCart)],
        },
        onEnter: [
          '$timeout',
          'analytics',
          'cartService',
          'checkoutAnalytics',
          'configConstants',
          '$rootScope',
          '$state',
          (
            $timeout,
            analytics,
            cartService,
            checkoutAnalytics,
            configConstants,
            $rootScope,
            $state
          ) => {
            if (newCheckoutConfig.cartReviewPage.enabled) {
              $timeout(() => $state.go('main.checkout.cartV2', null, { location: 'replace' }))
            } else if (!$rootScope.modifyingOrder) {
              _viewCartAnalytics({ $rootScope, $timeout, checkoutAnalytics })
            }
          },
        ],
      })

      // Cart v2, This needs to be seperate as we need the v1 state to exist so that it can redirect
      // or else we will always be redirected to the 404 as the v1 state won't exist before we can go to the right state.
      runtimeRoutes.addState('main.checkout.cartV2', {
        url: CHECKOUT_ROUTES.V2.CART,
        views: {
          checkout: {
            template: require('@app/angular/components/checkout/views/checkoutCart.html'),
            controller: 'CheckoutCartController',
          },
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          title: ['titleService', titleService => titleService.setTitle(lang.values.reviewCart)],
        },
        onEnter: [
          '$timeout',
          'analytics',
          'cartService',
          'checkoutAnalytics',
          'configConstants',
          '$rootScope',
          '$state',
          (
            $timeout,
            analytics,
            cartService,
            checkoutAnalytics,
            configConstants,
            $rootScope,
            $state
          ) => {
            if (!newCheckoutConfig.cartReviewPage.enabled) {
              $timeout(() => $state.go('main.checkout.cart', null, { location: 'replace' }))
            } else if (!$rootScope.modifyingOrder) {
              _viewCartAnalytics({ $rootScope, $timeout, checkoutAnalytics })
            }
          },
        ],
      })

      // Manual Upsell
      runtimeRoutes.addState('main.checkout.manualUpsell', {
        url: CHECKOUT_ROUTES.V1.UPSELL,
        data: {
          requireRegistered: true,
        },
        views: {
          checkout: {
            template: require('@app/angular/components/checkout/views/checkoutManualUpsell.html'),
            controller: 'CheckoutManualUpsellController',
          },
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          title: [
            'titleService',
            function (titleService) {
              return titleService.setTitle(lang.values.upsellTitle)
            },
          ],
        },
        onEnter: [
          'analytics',
          'cartService',
          'checkoutAnalytics',
          '$rootScope',
          'upsellProducts',
          (analytics, cartService, checkoutAnalytics, $rootScope, upsellProducts) => {
            if (!$rootScope.modifyingOrder) {
              checkoutAnalytics.enterCheckoutFlow()
              checkoutAnalytics.enterCheckoutUpsell({ upsellProducts })
            }
          },
        ],
        onExit: [
          'cartService',
          'checkoutAnalytics',
          'upsellProducts',
          (cartService, checkoutAnalytics, upsellProducts) => {
            const upsellProductsInCart = cartService.getUpSellProductsInCart(upsellProducts)
            checkoutAnalytics.exitCheckoutUpsell({ upsellProducts: upsellProductsInCart })
          },
        ],
      })

      // Reservation
      runtimeRoutes.addState('main.checkout.reservation', {
        url: '/reservation',
        data: {
          requireRegistered: true,
          requireICAccountLink: true,
        },
        views: {
          checkout: {
            template: require('@app/angular/components/checkout/views/checkoutReservation.html'), // This is the only line that differs from reservationRoutes
            controller: 'ReservationController',
          },
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          title: [
            'titleService',
            function (titleService) {
              return titleService.setTitle(lang.values.schedule)
            },
          ],
        },
        onEnter: [
          'analytics',
          'cartService',
          'checkoutAnalytics',
          '$rootScope',
          (analytics, cartService, checkoutAnalytics, $rootScope) => {
            if (!$rootScope.modifyingOrder) {
              checkoutAnalytics.enterCheckoutFlow()
            }
          },
        ],
      })

      // Address step
      runtimeRoutes.addState('main.checkout.reservation.address', {
        url: '/address',
        views: {
          reservation: {
            template: require('@app/angular/components/reservation/views/fulfillmentAddress.html'),
            controller: 'FulfillmentAddressController',
          },
        },
        data: {
          requireRegistered: true,
          requireICAccountLink: true,
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          userProfile: [
            'auth',
            function (auth) {
              return auth.getProfile()
            },
          ],
        },
        onEnter: [
          '$state',
          '$timeout',
          'analytics',
          'auth',
          'checkoutAnalytics',
          'cartService',
          '$rootScope',
          function ($state, $timeout, analytics, auth, checkoutAnalytics, cartService, $rootScope) {
            if (!$rootScope.modifyingOrder) {
              checkoutAnalytics.enterCheckoutFlow()
              checkoutAnalytics.viewFulfillmentOptions()
            }
            $timeout(() => {
              if (auth.shouldUseGigyaAddressEdit()) {
                $state.go('main.checkout.reservation.confirmation')
              }
            })
          },
        ],
      })

      // Reservation confirmation
      runtimeRoutes.addState('main.checkout.reservation.confirmation', {
        url: '/confirmation',
        views: {
          reservation: {
            template: require('@app/angular/components/reservation/views/reservationConfirmation.html'),
            controller: 'ReservationConfirmationController',
          },
        },
        data: {
          requireRegistered: true,
          requireICAccountLink: true,
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          userProfile: [
            'auth',
            function (auth) {
              return auth.getProfile()
            },
          ],
        },
        onEnter: [
          '$rootScope',
          '$state',
          '$timeout',
          'analytics',
          'auth',
          'cartService',
          'checkoutAnalytics',
          function ($rootScope, $state, $timeout, analytics, auth, cartService, checkoutAnalytics) {
            $timeout(() => {
              if (auth.isDeliveryContextInvalid()) {
                $state.go('main.checkout.reservation.address')
              } else if (!$rootScope.modifyingOrder) {
                checkoutAnalytics.enterCheckoutFlow()
                checkoutAnalytics.confirmFulfillmentOptions()
              }
            })
          },
        ],
      })

      // Payment
      runtimeRoutes.addState('main.checkout.payment', {
        url: '/payment',
        views: {
          checkout: {
            template: require('@app/angular/components/checkout/views/checkoutPayment.html'),
            controller: 'CheckoutPaymentController',
          },
        },
        data: {
          requireRegistered: true,
          requireICAccountLink: true,
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          payment: [
            '$q',
            'Restangular',
            function ($q, Restangular) {
              const deferred = $q.defer()
              Restangular.one('cart/payment')
                .get()
                .then(
                  payment => {
                    deferred.resolve(payment)
                  },
                  () => {
                    deferred.resolve({}) // Handle 404 when no payment present
                  }
                )
              return deferred.promise
            },
          ],
          title: [
            'titleService',
            function (titleService) {
              return titleService.setTitle(lang.values.pay)
            },
          ],
          userProfile: [
            'auth',
            function (auth) {
              return auth.getProfile()
            },
          ],
        },
        onEnter: [
          '$state',
          '$timeout',
          'analytics',
          'auth',
          'cartService',
          'checkoutAnalytics',
          'reservationService',
          '$rootScope',
          function (
            $state,
            $timeout,
            analytics,
            auth,
            cartService,
            checkoutAnalytics,
            reservationService,
            $rootScope
          ) {
            $timeout(() => {
              // Fetch and reload state if cart hasn't loaded yet. Edge case if window is refreshed.;
              if (!cartService.cart) {
                cartService.fetch().then(() => $state.reload())
              } else if (!cartService.cart.items.length) {
                $state.go('main.checkout.cart')
                // Redirect to address/reservation page if no timeslot or delivery mode but invalid state.
              } else if (!reservationService.reservation || auth.isDeliveryContextInvalid()) {
                $state.go('main.checkout.reservation.address')
              } else if (!$rootScope.modifyingOrder) {
                checkoutAnalytics.enterCheckoutFlow()
                checkoutAnalytics.viewPaymentOptions({ payment: {} })
              }
            })
          },
        ],
      })

      // Confirmation
      runtimeRoutes.addState('main.checkout.confirmation', {
        url: '/confirmation/:orderId',
        views: {
          checkout: {
            template: require('@app/angular/components/checkout/views/checkoutConfirmation.html'),
            controller: 'CheckoutConfirmationController',
          },
        },
        data: {
          requireRegistered: true,
          requireICAccountLink: true,
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          order: [
            '$stateParams',
            'Restangular',
            function ($stateParams, Restangular) {
              return Restangular.one('orders', $stateParams.orderId).get()
            },
          ],
          // We don't want any of the progress segments to be clickable/linkable on the checkout confirmation page
          modifiedProgressSegments: [
            'progressSegments',
            progressSegments => _.map(progressSegments, segment => ({ ...segment, sref: null })),
          ],
          title: [
            'titleService',
            function (titleService) {
              return titleService.setTitle(lang.values.orderComplete)
            },
          ],
        },
        onEnter: [
          'checkoutAnalytics',
          'order',
          function (checkoutAnalytics) {
            checkoutAnalytics.exitCheckoutFlow({}, true)
          },
        ],
      })

      // Upsell v2
      runtimeRoutes.addState('main.checkout.customUpsell', {
        url: CHECKOUT_ROUTES.V2.UPSELL,
        data: {
          requireRegistered: true,
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          title: [
            'titleService',
            titleService => {
              return titleService.setTitle(lang.values.upsellTitle)
            },
          ],
        },
        views: {
          'fixed-content@': {
            template: require('@app/angular/components/checkout/views/customUpsell.html'),
            controller: 'CheckoutManualUpsellController',
          },
        },
        onEnter: [
          '$timeout',
          'checkoutAnalytics',
          'upsellProducts',
          ($timeout, checkoutAnalytics, upsellProducts) => {
            checkoutAnalytics.enterCheckoutFlow()
            checkoutAnalytics.enterCheckoutUpsell({ upsellProducts })
          },
        ],
        onExit: [
          'cartService',
          'checkoutAnalytics',
          'upsellProducts',
          (cartService, checkoutAnalytics, upsellProducts) => {
            const upsellProductsInCart = cartService.getUpSellProductsInCart(upsellProducts)
            checkoutAnalytics.exitCheckoutUpsell({ upsellProducts: upsellProductsInCart })
          },
        ],
      })

      // Checkout v2 Page
      runtimeRoutes.addState('main.checkout.checkoutPage', {
        url: CHECKOUT_ROUTES.V2.CHECKOUT_REVIEW,
        data: {
          requireRegistered: true,
        },
        params: {
          initialStep: null,
        },
        resolve: {
          oneCartCheckoutRedirect: oneCartCheckoutRedirectFunc,
          initialStep: ['$stateParams', $stateParams => $stateParams.initialStep],
          title: ['titleService', titleService => titleService.setTitle(lang.values.checkout)],
          // Make sure PII session hasn't expired, will cause a stateChangeError if so.
          userProfile: ['auth', auth => auth.getProfile()],
        },
        views: {
          'fixed-content@': {
            template: require('@app/angular/components/checkout/views/checkoutPage.html'),
            controller: 'CheckoutPageController',
          },
        },
        onEnter: [
          '$state',
          '$timeout',
          'analytics',
          'cartService',
          'checkoutAnalytics',
          function ($state, $timeout, analytics, cartService, checkoutAnalytics) {
            $timeout(() => {
              // Fetch and reload state if cart hasn't loaded yet. Edge case if window is refreshed.
              if (!cartService.cart) {
                cartService.fetch().then(() => $state.reload())
              } else if (!cartService.cart.items.length) {
                $state.go('main.checkout.cart')
              } else {
                checkoutAnalytics.enterCheckoutFlow()
                checkoutAnalytics.enterCheckoutReview()
              }
            })
          },
        ],
        onExit: [
          'checkoutService',
          checkoutService => {
            checkoutService.isCartValidated = false
          },
        ],
      })

      runtimeRoutes.addState('main.checkout.icExpressOrderConfirmation', {
        url: '/order/{orderId}/subscription',
        resolve: {
          title: [
            'titleService',
            titleService => {
              return titleService.setTitle(lang.values.icExpress.postCheckoutPage.title)
            },
          ],

          order: [
            '$stateParams',
            'oneCartService',
            function ($stateParams, oneCartService) {
              return order.api.orders.get($stateParams.orderId, {
                one_cart_enabled: oneCartService.enabled(),
                user_timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
              })
            },
          ],
        },
        views: {
          'fixed-content@': {
            template: require('@app/angular/components/icExpress/views/postCheckoutCreditCardPage.html'),
            controller: 'IcExpressPostCheckoutCreditCardPageController',
            controllerAs: '$ctrl',
          },
        },
      })
    }

    const _viewCartAnalytics = ({ $rootScope, $timeout, checkoutAnalytics }) => {
      // This timeout is needed to resolve a race condition, the value of '$rootScope.previousState'
      // will update after the if statement is evaluated if there is no delay. So in the case of going
      // from the home page then to the cart this will evalute with an empty string even though the
      // previous state was the home page
      $timeout(() => {
        /**
         * Empty string here means that the application was reloaded by calling *$rootScope.reloadState*,
         * in this case, there's no need to trigger the cart.view event again. So we should not trigger the cart view event.
         * */
        if (!['', 'main.checkout.cartV2'].includes($rootScope.previousState)) {
          checkoutAnalytics.cartView()
        }
        checkoutAnalytics.exitCheckoutFlow()
      }, 10)
    }
  })
