import angular from 'angular'
import template from './couponUnclippedModal.html'

export default angular
  .module('CouponUnclippedModalServiceModule', [])

  // Service to provide access to launching the modal dialog

  .service('couponUnclippedModalService', function couponUnclippedModalService($uibModal) {
    this.open = function () {
      const modal = $uibModal.open({
        template,
        controller($scope, config, lang) {
          $scope.lang = lang
          $scope.config = config
        },
        keyboard: false,
        backdrop: 'static',
        windowClass: 'no-scrol',
      })

      return modal.result
    }
  })
