import { STRIPE } from '@app/angular/constants/paymentGateway'
import angular from 'angular'
import template from './stripeIntegration.html'

export default angular
  .module('StripePaymentServiceModule', [])

  .run(utilService => {
    utilService.loadScript(STRIPE.JS_FILE)
  })

  .config((configConstants, paymentGatewayProvider, stripeProvider) => {
    stripeProvider.setAPIKey(configConstants.stripe.apiKey)

    /**
     * Stripe can be used as an *external_auth* and *auth_capture* payment type
     */
    paymentGatewayProvider.registerGateway({
      name: 'stripe',
      key: 'auth_capture',
      template: template,
      serviceName: 'stripePayment',
      newCheckoutCssClass: 'provider-generic_styling',
      hideSaveButton: true,
      hideGiftCardLink: true,
    })

    paymentGatewayProvider.registerGateway({
      name: 'stripe',
      key: 'external_auth',
      template: template,
      serviceName: 'stripePayment',
      newCheckoutCssClass: 'provider-generic_styling',
      hideSaveButton: true,
      hideGiftCardLink: true,
    })
  })

  .service('stripePayment', function stripePayment($q, defaultPayments) {
    const service = this
    service.status = {}
    service.checkout = defaultPayments.checkout
  })

  // Forked from https://github.com/kareniel/angularjs-stripe-elements
  .provider('stripe', function stripeProvider() {
    // Global variable: Stripe

    this.apiKey = null

    this.setAPIKey = function (apiKey) {
      this.apiKey = apiKey
    }

    this.$get = function () {
      return Stripe(this.apiKey)
    }
  })

  .directive('stripeElementDecorator', function () {
    return {
      restrict: 'A',
      link: link,
    }

    function link($scope, $element) {
      const $ctrl = $scope.$ctrl

      $scope.$on('$destroy', () => {
        $ctrl.instance.destroy()
      })

      $ctrl.instance.mount($element[0])
    }
  })

  .component('stripeElement', {
    template: '<div ng-class="{ \'ic-theme\': $ctrl.useIcTheme }" stripe-element-decorator></div>',
    controller: function () {},
    bindings: {
      instance: '<',
      useIcTheme: '<',
    },
  })
