import React, { memo, useState, useEffect, useMemo, useRef, useCallback } from 'react'
import PropTypes from 'prop-types'
import { keyframes } from '@emotion/core'

import {
  createStyles,
  Pager,
  Button,
  List,
  ListItem,
  utils,
  useMediaQuery,
  Checkbox,
  Text,
  Alert,
} from '@instacart/cocktail'
import { useConfig } from '@app/services/config/hooks'
import TimeSlot from './TimeSlot'
import Day from './Day'
import TimeSlotsLoading from './TimeSlotsLoading'

import {
  isEqualDay,
  isEqualTimeSlot,
  getUniqIdFromTimeSlot,
  getTimeSlotFromUniqId,
  isTimeSlotBetween,
  getTimeSlotId,
} from '../utils'
import { TIMESLOT_POLYTYPES } from '../constants'

const DAYS_PER_PAGE = 7
const TIMESLOTS_PER_PAGE = 6

const fadeIn = keyframes({
  '0%': {
    opacity: 0,
  },
  '100%': {
    opacity: 1,
  },
})

const NAVIGATION_KEY_STEPS_MAP = {
  ArrowUp: -1,
  ArrowLeft: -1,
  ArrowDown: 1,
  ArrowRight: 1,
  Enter: 0,
  Spacebar: 0,
}

const useStyles = createStyles(tokens => ({
  root: {
    width: '100%',
  },
  titleRow: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  titleRowWithoutCheckbox: {
    justifyContent: 'flex-end',
  },
  dayPager: {
    maxWidth: '240px',
    minWidth: '220px',
    [utils.mediaQuery.maxWidth(tokens.breakpoint.small)]: {
      display: 'none',
    },
  },
  day: {
    marginRight: tokens.size.spacing.xxSmall,
  },
  days: {
    whiteSpace: 'nowrap',
    overflow: 'auto',
    marginBottom: tokens.size.spacing.small,
    paddingTop: tokens.size.spacing.xxxSmall,
    paddingBottom: tokens.size.spacing.xxxSmall,
  },
  moreTimesButton: {
    width: '100%',
    borderRadius: tokens.border.radius.small,
    margin: tokens.size.spacing.none,
    padding: tokens.size.spacing.none,
    '&:hover': {
      backgroundColor: tokens.color.neutral.grey1,
    },
  },
  availabilityCheckbox: {
    padding: utils.toPx`${tokens.size.spacing.medium} 0`,
  },
  noTimeslotsHeader: {
    paddingBottom: tokens.size.spacing.xxSmall,
  },
  timeslot: {
    animation: `${fadeIn} 0.2s ease 1`,
  },
  timeslotListItem: {
    padding: '0',
  },
  timeslotsList: {
    listStyle: 'none',
    padding: '0',
  },
  noTimeslotsDayMessage: {
    fontSize: tokens.size.font.body,
    padding: tokens.size.spacing.small,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  goToCartButton: {
    padding: tokens.size.spacing.small,
    display: 'block',
  },
  rxWarning: {
    borderRadius: tokens.border.radius.regular,
    backgroundColor: `${tokens.color.ui.warning} !important`,
    color: `${tokens.color.text.primary} !important`,
    textAlign: 'center',
    padding: tokens.size.spacing.small,
    fontWeight: tokens.font.weight.bold,
  },
}))

const defaultLang = {
  availabilityCheckboxLabel: 'Available Timeslots Only',
  moreTimes: 'More Times',
  nextWeek: 'Next Week',
  previousWeek: 'Previous Week',
  changeWeek: 'Change Week',
  noTimeslotAvailable: 'No Timeslot Available',
  noTimeslotsForDayError: 'There are no time slots available on this day.',
  noTimeslotsAvailableMessage:
    'Demand is higher than normal right now, so service times may not be available. Please check back later.',
  goToCart: 'Go to cart',
  rxWarning: 'Recipient must have an ID and be 18+ to accept prescriptions',
  aria: {
    daySelection: 'Day selection',
  },
}

const TimeSlots = props => {
  const {
    timeSlots,
    onTimeSlotSelected,
    onDaySelected,
    onGoToCart,
    onWeekChanged,
    selectedTimeSlot,
    i18n,
    readOnly,
    error,
    alwaysShowAvailabilityCheckbox,
    availabilityCheckboxThreshold,
    isLoading,
    hasRxItem,
    ...restProps
  } = props
  const lang = { ...defaultLang, ...i18n }
  const styles = useStyles(props)
  const isSmallBreakpoint = useMediaQuery(tokens =>
    utils.mediaQuery.maxWidth(tokens.breakpoint.small)
  )
  const firstTimeSlotAfterMoreTimes = useRef(null)
  const timeslotsContainer = useRef(null)
  const dayTabs = useRef(null)

  const days = useMemo(() => {
    return [
      ...(timeSlots.find(timeSlot => timeSlot.timeslot.polytype === TIMESLOT_POLYTYPES.ASAP)
        ? [TIMESLOT_POLYTYPES.ASAP]
        : []),
      ...new Set(timeSlots.map(({ fulfillment_date }) => fulfillment_date)),
    ]
  }, [timeSlots])

  const [page, setPage] = useState(1)
  const [onlyShowAvailableTimes, setOnlyShowAvailableTimes] = useState(true)
  const [showAllTimeSlots, setShowAllTimeSlots] = useState(false)
  const [selectedDay, setSelectedDay] = useState(null)
  const [attemptingToSelectTimeSlotId, setAttemptingToSelectTimeSlotId] = useState(null)

  const isTimeslotAvailable = timeslot => !(timeslot.unavailable || timeslot.during_blackout)

  // Gets the number of unavailable timeslots, for each day, days that are entirely unavailable,
  // and max unavailable overall count for any given day.
  const unavailabilities = useMemo(
    () =>
      timeSlots.reduce(
        (
          { unavailableCountByDay, dayUnavailability, maxUnavailableCountByDay },
          { timeslot, fulfillment_date: dayKey }
        ) => {
          const isAvailable = isTimeslotAvailable(timeslot)

          const unavailableCount = (unavailableCountByDay[dayKey] || 0) + (isAvailable ? 0 : 1)

          // A day is unavailable if all timeslots for that day are unavailable.
          const currentDayUnavailability =
            dayUnavailability[dayKey] === undefined || dayUnavailability[dayKey]
              ? !isAvailable
              : false

          return {
            dayUnavailability: { ...dayUnavailability, [dayKey]: currentDayUnavailability },
            unavailableCountByDay: { ...unavailableCountByDay, [dayKey]: unavailableCount },
            maxUnavailableCountByDay: Math.max(unavailableCount, maxUnavailableCountByDay),
          }
        },
        { dayUnavailability: {}, unavailableCountByDay: {}, maxUnavailableCountByDay: 0 }
      ),
    [timeSlots]
  )

  const getFirstAvailableDay = useCallback(
    (currentPage = page) => {
      const daysForPage = days.slice((currentPage - 1) * DAYS_PER_PAGE, currentPage * DAYS_PER_PAGE)

      return (
        daysForPage.reduce((availableDay, day) => {
          return !availableDay && !unavailabilities.dayUnavailability[day] ? day : availableDay
        }, null) || daysForPage[0]
      )
    },
    [unavailabilities, days, page]
  )

  useEffect(() => {
    if (selectedDay) return

    if (selectedTimeSlot && selectedTimeSlot.timeslot.polytype === TIMESLOT_POLYTYPES.ASAP) {
      setSelectedDay(TIMESLOT_POLYTYPES.ASAP)
    } else if (selectedTimeSlot && isTimeSlotBetween(selectedTimeSlot, timeSlots)) {
      setSelectedDay(selectedTimeSlot?.fulfillment_date)
    } else {
      setSelectedDay(getFirstAvailableDay())
    }
  }, [days, selectedTimeSlot, timeSlots, selectedDay, getFirstAvailableDay])

  useEffect(() => {
    if (attemptingToSelectTimeSlotId) {
      const attemptingToSelectTimeSlot = getTimeSlotFromUniqId(
        attemptingToSelectTimeSlotId,
        timeSlots
      )
      if (
        isEqualTimeSlot(attemptingToSelectTimeSlot, selectedTimeSlot) ||
        isEqualTimeSlot(attemptingToSelectTimeSlot, error?.payload?.invalid_params?.timeslot)
      ) {
        setAttemptingToSelectTimeSlotId(null)
      }
    }
  }, [selectedTimeSlot, attemptingToSelectTimeSlotId, timeSlots, error])

  useEffect(() => {
    return firstTimeSlotAfterMoreTimes?.current?.focus()
  }, [showAllTimeSlots])

  const createDaysItems = (newPage = 1, showAllDays = false) => {
    const visibleDays = !showAllDays
      ? days.slice((newPage - 1) * DAYS_PER_PAGE, newPage * DAYS_PER_PAGE)
      : days

    return visibleDays.map((day, i) => {
      return (
        <Day
          key={day}
          day={day}
          css={styles.day}
          onSelected={event => {
            const step = NAVIGATION_KEY_STEPS_MAP[event.key]
            const selectedDayIndex = visibleDays.findIndex(e => e === selectedDay)
            const selectDay = visibleDays[selectedDayIndex + step] || day

            event.stopPropagation()
            event.preventDefault()

            setShowAllTimeSlots(false)
            setSelectedDay(selectDay)
            onDaySelected(selectDay)

            if (step === 0 && event.type === 'keydown') {
              timeslotsContainer.current.focus()
            } else if (typeof step === 'number') {
              dayTabs.current.querySelector(`#reservation-select-day-${selectDay}`).focus()
            }
          }}
          isUnavailable={unavailabilities.dayUnavailability[day]}
          isSelected={selectedDay === day}
          i18n={lang.day}
          data-test={`day-${i}`}
          id={`reservation-select-day-${day}`}
          role="tab"
          aria-controls={`timeslots-${day}`}
          {...restProps}
        />
      )
    })
  }

  const timeSlotsDay = timeSlots.filter(
    ({ fulfillment_date, timeslot }) =>
      isEqualDay(fulfillment_date, selectedDay) || timeslot.polytype === selectedDay
  )
  const handleTimeSlotSelected = timeSlot => {
    setAttemptingToSelectTimeSlotId(getUniqIdFromTimeSlot(timeSlot))
    onTimeSlotSelected(timeSlot)
  }

  const showAvailabilityCheckbox = useMemo(() => {
    if (alwaysShowAvailabilityCheckbox) return true

    if (!availabilityCheckboxThreshold) return false

    return unavailabilities.maxUnavailableCountByDay >= availabilityCheckboxThreshold
  }, [unavailabilities, alwaysShowAvailabilityCheckbox, availabilityCheckboxThreshold])

  const filteredTimeslotsForDay = timeSlotsDay.filter(
    ({ timeslot }) =>
      !(showAvailabilityCheckbox && onlyShowAvailableTimes) || isTimeslotAvailable(timeslot)
  )

  const {
    shoppingContextProcess: {
      pickup: { showTimeslotFeeAsFree: showTimeslotPickupFeeAsFree },
    },
  } = useConfig()
  const createTimeSlotItems = (showAll = false) => {
    return (
      filteredTimeslotsForDay
        .filter(({ timeslot }) =>
          selectedDay === TIMESLOT_POLYTYPES.ASAP
            ? timeslot.polytype === TIMESLOT_POLYTYPES.ASAP
            : timeslot.polytype !== TIMESLOT_POLYTYPES.ASAP
        )
        .slice(0, showAll ? filteredTimeslotsForDay.length : TIMESLOTS_PER_PAGE)
        // Sort the time slots so that the time slots within_hours are at the top
        .sort((a, b) =>
          a.timeslot.within_hours && b.timeslot.within_hours
            ? 0
            : a.timeslot.within_hours
            ? -1
            : b.timeslot.within_hours
            ? 1
            : 0
        )
        .map((timeSlot, i) => {
          const timeSlotProps = { timeSlot, readOnly, showTimeslotPickupFeeAsFree }
          return (
            <ListItem css={styles.timeslotListItem} key={getTimeSlotId(timeSlot)}>
              <TimeSlot
                {...timeSlotProps}
                isSelected={isEqualTimeSlot(selectedTimeSlot, timeSlot)}
                onSelected={handleTimeSlotSelected}
                i18n={lang.timeSlot}
                data-test={`time-slot-${i}`}
                error={
                  error && isEqualTimeSlot(error.payload?.invalid_params?.timeslot, timeSlot)
                    ? error
                    : null
                }
                isAttemptingToSelect={isEqualTimeSlot(
                  getTimeSlotFromUniqId(attemptingToSelectTimeSlotId, timeSlots),
                  timeSlot
                )}
                ref={i === TIMESLOTS_PER_PAGE ? firstTimeSlotAfterMoreTimes : null}
                css={styles.timeslot}
                {...restProps}
              />
            </ListItem>
          )
        })
    )
  }

  if (isLoading) return <TimeSlotsLoading styles={styles} />

  return timeSlots.length > 0 ? (
    <div css={styles.root} {...restProps}>
      {hasRxItem ? <p css={styles.rxWarning}>{lang.rxWarning}</p> : <></>}

      <div css={[styles.titleRow, !showAvailabilityCheckbox ? styles.titleRowWithoutCheckbox : {}]}>
        {showAvailabilityCheckbox ? (
          <Checkbox
            id="availability-checkbox"
            data-test="availability-checkbox"
            checked={onlyShowAvailableTimes}
            onChange={event => setOnlyShowAvailableTimes(event.target.checked)}
            inputSize="small"
            css={styles.availabilityCheckbox}
          >
            {lang.availabilityCheckboxLabel}
          </Checkbox>
        ) : (
          <></>
        )}

        <Pager
          currentPage={page}
          pageCount={Math.ceil(days.length / DAYS_PER_PAGE)}
          onPageClick={newPage => {
            setShowAllTimeSlots(false)
            setPage(newPage)
            // This sets the day to the first day of the new week
            setSelectedDay(getFirstAvailableDay(newPage))
            onWeekChanged({ currentPage: page, newPage })
          }}
          css={styles.dayPager}
          showPageCount={false}
          previousLinkLabel={lang.previousWeek}
          nextLinkLabel={lang.nextWeek}
          aria-label={lang.changeWeek}
          data-test="time-slots-pager"
        />
      </div>
      <div ref={dayTabs} css={styles.days} role="tablist" aria-label={lang.aria.daySelection}>
        {createDaysItems(page, isSmallBreakpoint)}
      </div>
      <div
        role="tabpanel"
        aria-labelledby={`reservation-select-day-${selectedDay}`}
        id={`timeslots-${selectedDay}`}
      >
        <List css={styles.timeslotsList} ref={timeslotsContainer}>
          {filteredTimeslotsForDay.length > 0 ? (
            createTimeSlotItems(showAllTimeSlots, selectedDay)
          ) : (
            <ListItem css={styles.noTimeslotsDayMessage}>{lang.noTimeslotsForDayError}</ListItem>
          )}
        </List>
      </div>
      {!showAllTimeSlots && filteredTimeslotsForDay.length > TIMESLOTS_PER_PAGE && (
        <Button
          iconAfter="icon-chevron-down"
          iconSize="small"
          variant="link"
          css={styles.moreTimesButton}
          onClick={() => setShowAllTimeSlots(true)}
          data-test="time-slots-more-times"
        >
          {lang.moreTimes}
        </Button>
      )}
    </div>
  ) : (
    <div>
      <Text css={styles.noTimeslotsHeader} variant="large">
        {lang.noTimeslotAvailable}
      </Text>
      <Alert data-test="empty-state-message" variant="warning">
        {lang.noTimeslotsAvailableMessage}
        <Button css={styles.goToCartButton} onClick={() => onGoToCart()}>
          {lang.goToCart}
        </Button>
      </Alert>
    </div>
  )
}

TimeSlots.propTypes = {
  /** The array of time slots */
  // eslint-disable-next-line react/forbid-prop-types
  timeSlots: PropTypes.array.isRequired,
  /** Callback for when a timeslot is selected */
  onTimeSlotSelected: PropTypes.func,
  /** Callback for when a day is selected */
  onDaySelected: PropTypes.func,
  /** Callback for the go to cart button is pressed */
  onGoToCart: PropTypes.func,
  /** Callback for when the week is changed */
  onWeekChanged: PropTypes.func,
  /** The currently selected time slot */
  // eslint-disable-next-line react/forbid-prop-types
  selectedTimeSlot: PropTypes.object,
  /** Custom lang object */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Makes the component look like text an stops callbacks */
  readOnly: PropTypes.bool,
  /** error to display inline with the timeslot that is unavailable */
  // eslint-disable-next-line react/forbid-prop-types
  error: PropTypes.object,
  /** If enabled, the toggle to show or hide availability will always be visible */
  alwaysShowAvailabilityCheckbox: PropTypes.bool,
  /** If the number of unavailable timeslots on any day passes this threshold,
   * then the toggle to show or hide availability will become visible.
   * Set to 0 to disable. */
  availabilityCheckboxThreshold: PropTypes.number,
  /** Whether to show loading state */
  isLoading: PropTypes.bool,
  /** Whether to show pertinent information for RX */
  hasRxItem: PropTypes.bool,
}

TimeSlots.defaultProps = {
  onDaySelected: () => {},
  onWeekChanged: () => {},
  alwaysShowAvailabilityCheckbox: false,
  availabilityCheckboxThreshold: 0,
  isLoading: false,
}

export default memo(TimeSlots)
