// Require users to be registered before allowing links / buttons to be clicked

import angular from 'angular'

export default angular
  .module('requireRegisteredUserComponentModule', [])
  .directive('requireRegisteredUser', function ($location, $state, auth, registrationService) {
    return {
      restrict: 'A',
      priority: 1,
      link(scope, el, attrs) {
        el.on('click', e => {
          // If user is not registered, prevent links from firing
          // then prompt them to register, continuing to their intended
          // destination afterward

          if (auth.user.status === 'trial') {
            e.preventDefault()
            e.stopPropagation()
            registrationService.open('login').then(() => {
              if (attrs.uiSref) {
                $state.go(attrs.uiSref)
              } else if (attrs.href) {
                $location.path(attrs.href)
              }
            })
          } else {
            // Perform some action on click if user is registered

            // eslint-disable-next-line no-lonely-if
            if (attrs.ifRegistered) {
              scope.$apply(scope.$eval(attrs.ifRegistered))
            }
          }
        })
      },
    }
  })
