import angular from 'angular'
import template from './tipPickerModal.html'
import modalTemplate from './tipPickerModalTemplate.html'
import { FULFILLMENT_TYPES, MAX_TIP, CUSTOM_TIP_OPTION } from '../../../constants/checkout'

class TipPickerModal {
  constructor($uibModal, mobileService, cartService, config, lang, uibModalAriaLabelService) {
    this.modal = $uibModal
    this.modalAriaLabelService = uibModalAriaLabelService
    this.isMobile = mobileService.isMobile
    this.cartService = cartService
    this.config = config
    this.lang = lang
    this.maxTipValue = MAX_TIP
    this.customTipOption = CUSTOM_TIP_OPTION
  }

  open() {
    const parent = this

    this.modal.open({
      animation: true,
      template: modalTemplate,
      controller: [
        '$uibModalInstance',
        'auth',
        'checkoutAnalytics',
        'cartService',
        'checkoutService',
        'config',
        'lang',
        function (
          $uibModalInstance,
          auth,
          checkoutAnalytics,
          cartService,
          checkoutService,
          config,
          lang
        ) {
          Object.assign(this, {
            $uibModalInstance,
            auth,
            config,
            dismiss: $uibModalInstance.dismiss,
            error: '',
            lang,
            tip: {
              option: null,
              customAmount: null,
            },
            isLoading: false,
          })

          /**
           * Sets the initial tip value based on the cart and
           * configuration defaults
           *
           * @returns { undefined }
           */
          const setInitialTip = () => {
            const { cart } = cartService

            if (cart.tip_percent === null) {
              if (cart.tip_total !== null) {
                this.tip.option = parent.customTipOption
                this.tip.customAmount = Number(cart.tip_total)
              } else {
                this.tip.option = this.config.tipDefault
              }
            } else {
              this.tip.option = Number(cart.tip_percent)
            }

            checkoutAnalytics.viewTipOptions({ tip: this.tip })

            // Set tip percentage to a string to allow the radio button to behave as expected.
            if (this.tip.option != null) {
              this.tip.option = String(this.tip.option)
            }
          }

          /**
           * Calculates the tip amount based on cart *product_total* attribute
           * @param { string } value - the percentage of the tip
           *
           * @returns { number }
           */
          this.getTipAmount = percentage => {
            // check cart object for tip totals calculated from percents from BE
            const cartTotals = cartService.getCartTotals()

            if (cartTotals.tip_options?.[percentage]) {
              return cartTotals.tip_options[percentage]
            }

            // calculate tip amount from product_total and tip percent
            const total = _getProductTotal()
            return _calculateTipAmount(percentage, total)
          }

          /**
           * Get product total for tip calculation
           *
           * @returns { number }
           */
          const _getProductTotal = () => {
            let productTotal = cartService.cart.product_total
            if (this.config.tipUsesPreDiscountProductTotal) {
              productTotal = cartService.cart.pre_discount_product_total
            }
            return Math.floor(productTotal * 100)
          }

          /**
           * Calculate tip amount
           *
           * @returns { number }
           */
          const _calculateTipAmount = (percentage, total) => {
            return (percentage * total) / 100 / 100
          }

          /**
           * Returns the modal title based on the current fulfillment type
           *
           * @returns { string }
           */
          const getTitle = () => {
            const { tipPickup, tipDelivery } = this.lang.values

            return parent.fulfillmentType === FULFILLMENT_TYPES.PICKUP ? tipPickup : tipDelivery
          }

          /**
           * Applies the tip that was selected to the cart
           *
           * @returns { undefined }
           */
          this.applyTip = () => {
            const { tip } = this
            const { cart } = cartService
            const logEditTip = success => checkoutAnalytics.editTipOptions({ tip, success })
            if (tip.option !== parent.customTipOption) {
              cart.tip_percent = tip.option

              if (Number(tip.option) === 0) {
                cart.tip_total = 0
              }
            } else {
              if (!this.isCustomTipValid()) {
                this.error = this.lang.values.tipErrorMsg
                return
              }
              cart.tip_percent = null
              cart.tip_total = Number(tip.customAmount)
            }

            this.isLoading = true

            cartService
              .sync({
                validate: checkoutService.isCartValidated,
                validatePayload: {
                  ...checkoutService.getValidationPayload(auth.getContext().intent),
                },
              })
              .then(parent.onTipApplied)
              .then(this.dismiss)
              .then(() => logEditTip(true))
              .catch(err => {
                handleSyncError(err)
                logEditTip(false)
              })
              .finally(() => {
                this.isLoading = false
              })
          }

          /**
           * Handles cart sync errors
           *
           * @returns { undefined }
           */
          const handleSyncError = error => {
            const { message } = error.data || {}

            this.error = message || this.lang.values.defaultErrorMessage
          }

          /**
           * Validates the custom tip amount
           *
           * @returns { boolean }
           */
          this.isCustomTipValid = () => {
            const tipAmount = this.tip.customAmount
            return tipAmount >= 0 && tipAmount <= parent.maxTipValue
          }

          /**
           * Determine if tip option should be shown to customer
           *
           * @returns { boolean }
           */
          this.showTipOption = configTip => {
            if (configTip === 0) {
              return true
            }

            const tipAmount = this.getTipAmount(configTip)
            return (
              !this.config.tipCalculationMinimum || tipAmount >= this.config.tipCalculationMinimum
            )
          }

          /**
           * Gets the appropriate html for the tips description given the current intent mode
           * @returns {string}
           */
          this.getTipsFormDescriptions = () => {
            const { auth, lang } = this
            const { intent } = auth.getContext()
            const { tipsFormDescriptions } = lang.values
            return tipsFormDescriptions[intent] || tipsFormDescriptions.default
          }

          setInitialTip()
          this.title = getTitle()
          this.isMobile = parent.isMobile
          this.maxTipValue = parent.maxTipValue
        },
      ],
      keyboard: false,
      backdrop: 'static',
      controllerAs: '$ctrl',
      resolve: {
        ariaLabel: () => {
          parent.modalAriaLabelService.setLabel(parent.lang.values.tips)
        },
        cartService: parent.cartService,
        config: parent.config,
        lang: parent.lang,
      },
    })
  }
}

export default angular.module('TipPickerModalModule', []).component('tipPickerModal', {
  bindings: {
    label: '@',
    fulfillmentType: '<',
    onTipApplied: '&',
  },
  template,
  controller: TipPickerModal,
})
