import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('ProgressIndicatorComponentModule', [])
  .component('progressIndicator', {
    bindings: {
      segments: '<',
      currentSegment: '@',
    },
    controller(mobileService, lang) {
      const { checkoutProgress } = lang.values
      this.isMobile = mobileService.isMobile

      this.currentSegmentIndex = _.findIndex(this.segments, { id: this.currentSegment })

      // Mark which segments are complete, and only allow complete segments to hold on to their sref
      this.segments = _.map(this.segments, (segment, index) => {
        if (index < this.currentSegmentIndex) {
          return {
            ...segment,
            complete: true,
            screenread: checkoutProgress.steps.complete,
          }
        }
        return {
          ...segment,
          complete: false,
          sref: null,
          screenread:
            index === this.currentSegmentIndex
              ? checkoutProgress.steps.inProgress
              : checkoutProgress.steps.incomplete,
        }
      })
    },
    template: `
      <div class="progress-indicator">
        <div
          ng-repeat="segment in $ctrl.segments"
          class="segment-container"
          ng-class="{ complete: segment.complete, current: $ctrl.currentSegmentIndex == $index }"
          ng-attr-style="width: {{ 100 / $ctrl.segments.length }}%; z-index: {{ $ctrl.segments.length - $index }};"
          aria-label="{{segment.label}} Segment"
          analytics-click="['progress bar segment', 'click', $index]">
          <a
            ng-if=":: !$ctrl.isMobile && segment.sref"
            ui-sref="{{segment.sref}}"
            class="segment"
            id="progress-{{segment.id}}">
            <span class="screen-reader-text">{{segment.screenread}}</span>
            <progress-segment-content label="{{:: segment.label}}" number="{{:: ($index + 1)}}"></progress-segment-content>
          </a>
          <span
            ng-if=":: $ctrl.isMobile || !segment.sref"
            class="segment"
            id="progress-{{segment.id}}">
            <span class="screen-reader-text">{{segment.screenread}}</span>
            <progress-segment-content label="{{:: segment.label}}" number="{{:: ($index + 1)}}"></progress-segment-content>
          </span>
        </div>
      </div>
    `,
  })
  .component('progressSegmentContent', {
    bindings: {
      label: '@',
      number: '@',
    },
    template: `
      <span class="number">{{$ctrl.number}}</span>
      <span class="icon" aria-hidden="true"></span>
      <span class="label">{{$ctrl.label}}</span>
    `,
  })
