import angular from 'angular'
import { MEMBERSHIP_STATUSES } from '@app/angular/constants/express'
import template from './icExpressTimeslot.html'

class IcExpressTimeslot {
  constructor($filter, config, express, lang) {
    Object.assign(this, {
      express,
      lang,
      $filter,
      expressConfig: config.icExpress,
    })
  }

  get remainingAmount() {
    return this.expressConfig.minimumOrderAmount - parseFloat(this.cartTotal)
  }

  get checkboxLabel() {
    const label = this.optedIn ? this.optedInLabel : this.label

    return this.lang.template(label, { name: this.expressConfig.name })
  }

  get label() {
    const { trialEligibleLabel, membershipEligibleLabel } = this.expressConfig.placements.timeslot

    return this.express.isTrialEligible() ? trialEligibleLabel : membershipEligibleLabel
  }

  get optedInLabel() {
    const { trialSelectedLabel, membershipSelectedLabel } = this.expressConfig.placements.timeslot

    return this.express.isTrialEligible() ? trialSelectedLabel : membershipSelectedLabel
  }

  /**
   * Returns if the user has an active TRIAL plan or MEMBERSHIP
   * @returns { boolean }
   */
  get isEnrolled() {
    return [MEMBERSHIP_STATUSES.TRIAL, MEMBERSHIP_STATUSES.MEMBERSHIP].includes(
      this.membershipStatus
    )
  }

  get isExpressEligible() {
    return this.express.isTrialEligible() || this.express.isMembershipEligible()
  }
}

export default angular
  .module('icExpressTimeslotComponentModule', [])
  .component('icExpressTimeslot', {
    template,
    bindings: {
      onChange: '&',
      cartTotal: '<',
      useIcTheme: '<',
      membershipStatus: '<',
      optedIn: '=',
    },
    controller: IcExpressTimeslot,
  })
