import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import { actions as searchActions } from '@app/services/search/slice'

export default angular
  .module('mobileNavServicesModule', [])
  .service('mobileNavService', function mobileNavService($ngRedux, $rootScope, utilService) {
    const service = this
    let removeTrapFocus = () => {}

    // bind searchActions to this(service)
    $ngRedux.connect(null, { ...searchActions })(service)

    service.handleEscKeyDown = event => {
      if (event.keyCode === 27 && service.slideOutMenuIsVisible) {
        service.hideSlideOutMenu()
        // Force angular to re-render
        $rootScope.$apply()
      }
    }

    service.slideOutMenuIsVisible = false

    service.hideSlideOutMenu = () => {
      service.slideOutMenuIsVisible = false
      removeTrapFocus()
      // eslint-disable-next-line no-unused-expressions
      document.querySelector('button.menu-toggle')?.focus()
      document.removeEventListener('keydown', service.handleEscKeyDown)
    }

    service.showSlideOutMenu = () => {
      service.slideOutMenuIsVisible = true
      const el = document.querySelector('#mobile-side-nav')

      if (el) {
        removeTrapFocus = utilService.trapFocusOn(el, true)
        document.addEventListener('keydown', service.handleEscKeyDown)
      }
    }

    service.setSlideOutMenu = val => {
      if (val) {
        service.showSlideOutMenu()
      } else {
        service.hideSlideOutMenu()
      }
    }

    service.toggleSlideOutMenu = () => {
      if (service.slideOutMenuIsVisible) {
        service.hideSlideOutMenu()
      } else {
        service.showSlideOutMenu()
      }
    }

    // TODO: Removal of event emitter after sideout menu conversion
    eventEmitter.on(EVENT_TYPES.TOGGLE_SLIDEOUT_MENU, () => {
      service.toggleSlideOutMenu()
      $rootScope.$apply()
    })
  })
  .service('navService', function navService(auth, config, lang) {
    const service = this

    service.getCartListReviewItems = () => {
      const shoppingMode = auth.getCurrentShoppingMode()
      const { navigation } = config.cartReview
      const { params } = config.eComLight.homeRedirect || {}
      const navItemsConfig = navigation[shoppingMode] || navigation.default

      return navItemsConfig.map(item => {
        const itemParams =
          shoppingMode === SHOPPING_MODES.CATERING && item.state ? { ...params } : {}

        return {
          ...item,
          params: itemParams,
          label: lang.values[item.langKey] || item.langKey,
        }
      })
    }
  })
