import angular from 'angular'

export default angular.module('unataStoreidLabelModule', []).component('unataStoreidLabel', {
  bindings: {
    changeStoreLink: '<',
    isChildOfUnifiedHeader: '<',
  },
  controller(auth, config, mobileService, shoppingSelectorService) {
    this.authUser = auth.user
    this.config = config
    this.isMobile = mobileService.isMobile
    this.openShoppingSummary = () =>
      shoppingSelectorService.open(config.defaultSelectorService.desktopHomeContext)
  },
  template: `
      <a ng-if="!$ctrl.changeStoreLink" id="nav-shopping-selector-store" class="value store" ng-click="($ctrl.isChildOfUnifiedHeader === false || !$ctrl.mobile.isMobile ) && $ctrl.openShoppingSummary()">
        <span>
          {{$ctrl.authUser.store.name}}
        </span>
      </a>

      <span ng-if="$ctrl.changeStoreLink">
        {{$ctrl.authUser.store.name}}  <a id="nav-shopping-selector-store" ng-click="$ctrl.openShoppingSummary()" class="change-store-link">Change</a>
      </span>
    `,
})
