import angular from 'angular'

export default angular
  .module('UnataMobileHeaderHomeButtonModule', [])
  .component('unataMobileHeaderHomeButton', {
    controller($state, config, mobileService, lang) {
      this.config = config
      this.isMobile = mobileService.isMobile
      this.$state = $state
      this.lang = lang
    },
    template: `
      <a ng-click="$ctrl.$state.go('main.home')" class="icon-home mobile-header-home-button" aria-label="{{::lang.value.gotoHomepage}}">
    `,
  })
