import angular from 'angular'
import { AUTH_ACTIONS } from '@app/angular/constants/authActions'

export default angular
  .module('LoyaltyOptInPlacementModule', [])
  .component('loyaltyOptInPlacement', {
    bindings: {
      isInfoBox: '<',
    },
    controller(auth, lang, registrationService, userService, $state) {
      const vm = this
      vm.lang = lang

      // Make sure that the loyalty cards are loaded.
      userService.getLoyaltyCards()

      vm.shouldShow = () => auth.user.status === 'trial' || !userService.loyaltyCards.length
      // The loyalty flow should go through the login process automatically if the user is not logged in.
      vm.proceedToAction = () =>
        registrationService
          .open(AUTH_ACTIONS.LOYALTY)
          .then(() => {
            return userService.fetchLoyaltyCards()
          })
          .then(() => {
            // Need to reload state for third party sign up on mobile because
            // instead of redirecting back to the app, the third party auth
            // closes in app browser window instead resulting in user going back to loyalty sign up view
            $state.reload('main.account.loyalty')
          })
    },
    template: `
      <div class="loyalty-opt-in-container" ng-class="{'message info-positive': $ctrl.isInfoBox}" ng-if="$ctrl.shouldShow()" data-test="loyalty-opt-in">
        <span class="icon-error" aria-hidden="true" ng-if="$ctrl.isInfoBox"></span>
        <a class="button call-to-action" ng-click="$ctrl.proceedToAction()">
          {{::$ctrl.lang.values.loyaltyOptInCta}}
        </a>
        <strong class="">{{::$ctrl.lang.values.loyaltyOptInHeader}}</strong>
        <span class="">{{::$ctrl.lang.values.loyaltyOptInDescription}}</span>
      </div>
    `,
  })
