import _ from 'lodash'
import angular from 'angular'
import { UNIT_OF_MEASURE } from '@app/angular/constants/uom'

export default angular
  .module('InvalidCartItemsDisplayComponentModule', [])
  .component('invalidCartItemsDisplay', {
    bindings: {
      validateResponse: '<',
      onDismiss: '&',
    },
    controller($q, $state, cartService, lang, imageURLService, mobileService) {
      const vm = this
      vm.lang = lang
      vm.imageURLService = imageURLService
      vm.mobile = mobileService
      vm.UNIT_OF_MEASURE = UNIT_OF_MEASURE
      vm.warningMessage = vm.validateResponse.message

      const unavailableItemsIds = vm.validateResponse.unavailable_item_ids
      const unavailableItems = _.filter(
        cartService.cart.items,
        i => unavailableItemsIds.indexOf(i.id) >= 0
      )

      vm.hasUnavailableItems = unavailableItems.length
      vm.unavailableItemsByCategory = _.groupBy(
        unavailableItems,
        item => item.store_product.categories[0].name
      )

      vm.removeUnavailableItems = () => {
        cartService
          .removeItems(unavailableItems)
          .then(() =>
            cartService.countItems() > 0
              ? cartService.validateCart()
              : $state.go('main.checkout.cart')
          )
          .catch(() => $state.go('main.checkout.cart'))

        vm.onDismiss()
      }

      vm.goBack = () => {
        vm.onDismiss()
        $state.go('main.checkout.cart')
      }
    },
    template: `
      <div class="compare-cart-modal modal-container">
        <div class="modal-header">
          {{::$ctrl.lang.values.warning}}
        </div>
        <div class="compare-cart-content modal-content">

          <div class="message warn">
            <span class="icon-warning" aria-hidden="true"></span>
            {{::$ctrl.warningMessage}}
          </div>

          <table class="table">
            <thead ng-if=":: (!$ctrl.mobile.isMobile && $ctrl.hasUnavailableItems)">
              <tr>
                <td>{{::$ctrl.lang.values.productName}}</td>
                <td>{{::$ctrl.lang.values.quantity}}</td>
                <td>{{::$ctrl.lang.values.total}}</td>
              </tr>
            </thead>
            <tbody ng-repeat="(category, items) in ::$ctrl.unavailableItemsByCategory">

              <!-- Category Heading -->

              <tr class="category">
                <td colspan="3">{{category}}</td>
              </tr>

              <!-- Product -->

              <tr ng-repeat="item in items track by $index" class="products-list" ng-if="item.quantity > 0 && (!item.store_product.product_type || item.store_product.product_type !== 'subscription')">

                <!-- Product name / image -->

                <td>
                  <div class="product">
                    <div class="image" ng-attr-style="background-image: url('{{::$ctrl.imageURLService.getProductImageURL(item.store_product, 'small')}}')" analytics-click="['cart product link', 'click']"></div>
                    <div class="meta">
                      <div class="prod-name">{{item.store_product.name}}</div>
                      <div ng-if="item.hasProductInventoryChange" class="inventory-diff">{{::$ctrl.lang.values.outOfStock}}</div>
                      <div ng-if="item.hasProductBlackout" class="product-blackout">{{item.blackoutMessage}}</div>
                      <div ng-if="item.hasconfigurableProductLeadtime" class="product-blackout">{{item.configurableProductLeadtimeMessage}}</div>
                      <div ng-if="item.hasProductPricingChange" class="pricing-diff">{{::$ctrl.lang.values.pricingChange}}</div>
                    </div>
                  </div>
                </td>

                <td>
                  <div class="quantity">
                    {{:: $ctrl.mobile.isMobile ? $ctrl.lang.values.values.qty + ' ' : ''}}
                    <span class="quant-amnt">{{item.quantity}}</span>
                    <span class="quant-unit">{{item.store_product.ordering_parameters.display_name.toLowerCase() != $ctrl.UNIT_OF_MEASURE.EACH ? item.store_product.ordering_parameters.display_name : ''}}</span>
                  </div>
                </td>

                <td>
                  <div class="total">
                    <div class="total-txt">{{:: $ctrl.mobile.isMobile ? $ctrl.lang.values.total : ''}}</div>
                    <div class="total">{{item.unit_price | currency}} <span class="per-unit">/ {{item.store_product.display_uom}}</span></div>
                  </div>
                </td>
              </tr>
            </tbody>

          </table>

          <!-- If the user canProceed they will be given the option to continue or cancel from the dialog -->
          <!-- If user canProceed is not true they will be given the option to click 'Ok' and that will dismiss the modal -->

          <div class="mobile-bottom" ng-class="::{ 'row buttons-box': !$ctrl.mobile.isMobile, 'buttons': $ctrl.mobile.isMobile }" fixed-to-body>
            <button
              ng-click="$ctrl.goBack()"
              class="button" ng-class="::{ 'hollow': !$ctrl.mobile.isMobile, 'mobile-bottom dark': $ctrl.mobile.isMobile }"
              analytics-click="['invalidCartItems modal back button', 'click']">
              {{ $ctrl.lang.values.cancelAndBackToCart }}
            </button>
            <button
              ng-click="$ctrl.removeUnavailableItems()"
              class="button" ng-class="::{ 'mobile-bottom': $ctrl.mobile.isMobile }"
              analytics-click="['invalidCartItems modal continue button', 'click']">
              {{ $ctrl.lang.values.removeAndContinue }}
            </button>
          </div>
        </div>
      </div>

    `,
  })
