import angular from 'angular'
import dayjs from 'dayjs'
import {
  EXPRESS_SUBSCRIPTION_PLAN_TERMS,
  IC_PLACEMENTS,
  IC_MEMBERSHIP_PLANS,
  MESSAGE_PLACEMENTS,
  MEMBERSHIP_STATUSES,
  EXPIRATION_DATE_FORMAT as DATE_FORMAT,
  SUBSCRIPTION_TYPES,
} from '@app/angular/constants/express'
import { PAYMENT_CONTEXTS } from '@app/angular/constants/payments'
import interpolate from '@instacart/enterprise-services/lang/interpolate'
import addCreditCardModalTemplate from './views/addCreditCardModal.html'
import expressPlansModalTemplate from './views/expressPlansModal.html'
import expressPlansModalTemplateNew from './views/expressPlansModalNew.html'
import expressGiftCardRedeemModalTemplate from './views/expressGiftCardRedeemModal.html'
import expressPromoPlanChangeModalTemplate from './views/expressPromoPlanChangeModal.html'

const CREDIT_CARD_AUTH_TYPE = 'auth_capture'

export default angular
  .module('ICExpressProfilePageControllerModule', [])
  .controller(
    'IcExpressProfilePageController',
    function (
      $filter,
      $rootScope,
      $scope,
      $state,
      $uibModal,
      Restangular,
      auth,
      config,
      express,
      icExpressModalService,
      lang,
      mobileService
    ) {
      Object.assign($scope, {
        auth,
        lang,
        config,
        express,
        icExpressModalService,
        creditCards: [],
        MESSAGE_PLACEMENTS,
        selectedCreditCard: null,
        IC_PLACEMENTS,
        mobileService,
        MEMBERSHIP_STATUSES,
        isLoading: true,
        paymentContext: PAYMENT_CONTEXTS.EXPRESS,
      })

      const { icExpress } = config

      const PLANS = {
        [EXPRESS_SUBSCRIPTION_PLAN_TERMS.TRIAL]: {
          id: EXPRESS_SUBSCRIPTION_PLAN_TERMS.TRIAL,
          name: icExpress.plans.trial.name,
          description: icExpress.plans.trial.description,
          period: icExpress.plans.trial.period,
          minimumOrder: icExpress.minimumOrderAmount,
          recurrence: icExpress.plans.trial.recurrence,
        },
        [EXPRESS_SUBSCRIPTION_PLAN_TERMS.YEARLY]: {
          id: EXPRESS_SUBSCRIPTION_PLAN_TERMS.YEARLY,
          name: icExpress.plans.yearly.name,
          description: icExpress.plans.yearly.description,
          period: icExpress.plans.yearly.period,
          minimumOrder: icExpress.minimumOrderAmount,
          recurrence: icExpress.plans.yearly.recurrence,
        },
        [EXPRESS_SUBSCRIPTION_PLAN_TERMS.MONTHLY]: {
          id: EXPRESS_SUBSCRIPTION_PLAN_TERMS.MONTHLY,
          name: icExpress.plans.monthly.name,
          description: icExpress.plans.monthly.description,
          period: icExpress.plans.monthly.period,
          minimumOrder: icExpress.minimumOrderAmount,
          recurrence: icExpress.plans.monthly.recurrence,
        },
      }

      /**
       * This const exists to represent plans that are not mapped in the Unata platform
       */
      const GENERIC_PLANS = {
        TRIAL: {
          name: `${icExpress.name} ${lang.values.icExpress.trial}`,
        },
        MEMBERSHIP: {
          name: `${icExpress.name} ${lang.values.icExpress.membership}`,
        },
      }

      $scope.membershipPlans = []

      const _getSubscriptionPlan = plan => {
        if (plan?.term_length) {
          return { ...PLANS[plan.term_length], ...plan }
        }
        return { ...PLANS[EXPRESS_SUBSCRIPTION_PLAN_TERMS.TRIAL], ...plan }
      }

      /**
       * Sets the membership plans the user can subscribe to
       * @param { Array } membershipPlans
       * @returns { undefined }
       */
      $scope.setMembershipPlans = async membershipPlans => {
        $scope.membershipPlans = membershipPlans
          .filter(plan => plan.polytype !== EXPRESS_SUBSCRIPTION_PLAN_TERMS.TRIAL)
          .map(subscriptionPlan => _getSubscriptionPlan(subscriptionPlan))
      }

      /**
       * Toggles the Express reminder
       *
       * @returns {Promise}
       */
      $scope.toggleReminder = () => {
        return express
          .toggleReminder()
          .then(express.getCurrentSubscription)
          .then(membership => {
            $scope.showReminderMessage = !express.getReminderDate()._isValid
            $scope.reminderDate = express.getReminderDate().format(DATE_FORMAT)

            express.setMessage(
              lang.values.reminderUpdated,
              membership.subscription?.send_reminder_on
                ? lang.values.reminderTurnedOn
                : lang.values.reminderTurnedOff,
              null,
              MESSAGE_PLACEMENTS.PAYMENT
            )
          })
      }

      const _safelyGetSubscription = () => {
        return express.userCurrentSubscription?.subscription || {}
      }

      $scope.getCurrentSubscription = () => {
        const subscription = _safelyGetSubscription()
        const subscriptionPlan = _getSubscriptionPlan(subscription.subscription_plan)

        if (!subscriptionPlan) {
          const customPlan = express.isTrial() ? GENERIC_PLANS.TRIAL : GENERIC_PLANS.MEMBERSHIP

          return { ...customPlan, description: subscription?.subscription_plan?.description }
        }

        return subscriptionPlan
      }

      $scope.getNextPlanDate = () => {
        const subscription = _safelyGetSubscription()

        return dayjs(subscription.end_date)
      }

      $scope.getNextSubscriptionPlan = () => {
        const subscription = _safelyGetSubscription()
        return _getSubscriptionPlan(subscription?.next_subscription_plan) || {}
      }

      $scope.isTrial = express.isTrial
      $scope.isCoupon = express.isCoupon

      $scope.getTrialRemainingDays = () => {
        const duration = dayjs($scope.getNextPlanDate()).diff(dayjs(), 'days')
        return Math.floor(duration)
      }

      $scope.hideReminderMessage = () => {
        $scope.showReminderMessage = false
      }

      $scope.getNextPaymentString = () => {
        const nextSubscriptionPlan =
          express.userCurrentSubscription?.subscription?.next_subscription_plan

        if (!nextSubscriptionPlan) return

        const { price, term_length } = nextSubscriptionPlan

        return lang.templateAsString(lang.values.nextPaymentOn, {
          date: $scope.membershipEndDate(),
          amount: $filter('currency')(price),
          duration: term_length,
        })
      }

      $scope.membershipEndDate = () => express.getMembershipEndDate().format(DATE_FORMAT)

      $scope.getNextPlanString = () => {
        const nextSubscriptionPlan =
          express.userCurrentSubscription?.subscription?.next_subscription_plan

        if (!nextSubscriptionPlan) return

        return lang.template(lang.values.instacartPlan, {
          duration: lang.values[IC_MEMBERSHIP_PLANS[nextSubscriptionPlan.term_length]] || '',
        })
      }

      $scope.getReminderString = () => {
        if (express.userCurrentSubscription?.subscription?.id) {
          return lang.template(lang.values.reminderSentToYou, {
            date: express.getReminderDate().format(DATE_FORMAT),
          })
        }
      }

      express.isExpiredMembership().then(resp => {
        if (resp) {
          $scope.icExpressModalService.open()
        }
      })

      const vm = this

      /**
       * Loads initial data required for rendering the Express profile page
       */
      this.loadInitialData = () => {
        express
          .getCurrentSubscription()
          .then(() => {
            $scope.reminderDate = express.getReminderDate().format(DATE_FORMAT)
          })
          .finally(() => {
            $scope.isLoading = false
          })

        express
          .fetchEligibleSubscriptionPlans(true)
          .then(subscriptionPlans => $scope.setMembershipPlans(subscriptionPlans))

        this.loadCreditCards()
      }

      /**
       * Fetch and set credit cards
       * @returns { Promise }
       */
      vm.loadCreditCards = () => {
        return Restangular.one('user')
          .post('payer', {
            payment_type: CREDIT_CARD_AUTH_TYPE,
            service_type: PAYMENT_CONTEXTS.EXPRESS,
            auth_capture: {},
          })
          .then(() => {
            return Restangular.one('user')
              .one('payment')
              .get({ payment_type: CREDIT_CARD_AUTH_TYPE, service_type: PAYMENT_CONTEXTS.EXPRESS })
          })
          .then(paymentInstruments => this.setCreditCards(paymentInstruments))
      }

      this.setCreditCards = paymentInstruments => {
        $scope.creditCards = paymentInstruments[CREDIT_CARD_AUTH_TYPE].payment_instruments
      }

      $scope.currentCreditCard = () => {
        return (
          $scope.creditCards.find(
            cc => cc.ext_id === express.userCurrentSubscription?.subscription?.ext_instrument_id
          ) || {}
        )
      }

      /**
       * Updates the Express payment method
       * @return { Promise }
       */
      $scope.updatePaymentMethod = payment => {
        const subscriptionId = express.userCurrentSubscription?.subscription?.id
        const { id, ext_id, ext_payer_id } = payment

        return express.updateSubscription({
          ext_id,
          ext_payer_id,
          credit_card_id: id,
          subscription_id: subscriptionId,
        })
      }

      $scope.openGiftCardRedeemModal = () => {
        return $uibModal.open({
          template: expressGiftCardRedeemModalTemplate,
          controller($uibModalInstance) {
            this.onCancel = () => {
              $uibModalInstance.close()
            }

            this.onSuccess = () => {
              $uibModalInstance.close()
            }
          },
          controllerAs: '$ctrl',
          keyboard: false,
          backdrop: 'static',
        }).result
      }

      /**
       * Opens the Stripe credit card modal form
       * @returns { undefined }
       */
      $scope.openAddCreditCardModal = () => {
        const $parentscope = $scope

        $uibModal.open({
          template: addCreditCardModalTemplate,
          controller($scope, $uibModalInstance, config, lang) {
            this.lang = lang
            this.config = config
            $scope.paymentContext = PAYMENT_CONTEXTS.EXPRESS

            const _handlePaymentUpdate = card => {
              express.setMessage(
                lang.values.paymentMethodUpdated,
                null,
                null,
                MESSAGE_PLACEMENTS.PAYMENT
              )
              $parentscope.creditCards = [...$parentscope.creditCards, card]
            }

            this.handleCardSelection = card => {
              if (card.ext_id === $parentscope.currentCreditCard().ext_id) {
                $uibModalInstance.dismiss()
                return
              }

              $parentscope.isLoading = true
              $uibModalInstance.dismiss()
              document.documentElement.scrollTop = 0

              $parentscope
                .updatePaymentMethod(card)
                .then(() => _handlePaymentUpdate(card))
                .finally(() => ($parentscope.isLoading = false))
            }
          },
          controllerAs: '$ctrl',
        })
      }

      $scope.isAutoRenew = () => express.userCurrentSubscription?.subscription?.autorenew

      /**
       * Returns an object with the status of the user current membership
       * @returns { object }
       */
      $scope.membershipStatus = () => {
        const status = express.getMembershipStatus()
        const autorenew = $scope.isAutoRenew()

        return {
          active: autorenew || express.isCoupon(),
          status,
        }
      }

      /**
       * Reverts the trial cancellation
       * @returns {Promise}
       */
      $scope.revertTrialCancellation = () => {
        const subscription_id = express.userCurrentSubscription?.subscription?.id

        return express
          .updateSubscription({
            cancel: false,
            autorenew: true,
            subscription_id,
          })
          .then(express.getCurrentSubscription)
      }

      $scope.changePlanFromModal = plan => {
        if (
          !plan ||
          express.userCurrentSubscription?.subscription.next_subscription_plan.id === plan.id
        ) {
          return
        }
        $scope.isLoading = true
        document.documentElement.scrollTop = 0
        express
          .updateMembershipPlan(plan.id)
          .then(async () => {
            let message = lang.values.planUpdate
            if (express.userCurrentSubscription?.subscription?.next_subscription_plan?.promo)
              message = config.lang.icExpress.profilePage.promoPlanChange.promoPlanSubscribeMessage
            else if (
              express.userCurrentSubscription?.subscription?.subscription_plan?.polytype ===
              SUBSCRIPTION_TYPES.TRIAL
            )
              message = lang.values.trialPlanUpdate

            express.setMessage(
              lang.values.membershipUpdated,
              lang.templateAsString(message, { plan: _getSubscriptionPlan(plan).name }),
              null,
              MESSAGE_PLACEMENTS.MEMBERSHIP
            )

            $scope.setMembershipPlans([])
            express
              .fetchEligibleSubscriptionPlans(true)
              .then(subscriptionPlans => $scope.setMembershipPlans(subscriptionPlans))
          })
          .finally(() => {
            $scope.isLoading = false
          })
      }

      /**
       * Opens the New Express plans modal
       */
      $scope.openPlansModalNew = async () => {
        const nonPromoPlans = await express.fetchEligibleSubscriptionPlans(false, true)
        const currentSubscription = (await express.getCurrentSubscription())?.subscription
        const nextPlan = currentSubscription?.next_subscription_plan
        $uibModal.open({
          template: expressPlansModalTemplateNew,
          controller($uibModalInstance) {
            this.plans = () => {
              const plansToShow = nonPromoPlans
              if (!plansToShow.find(plan => plan.id === nextPlan.id)) plansToShow.unshift(nextPlan)
              return plansToShow
            }

            this.currentSubscription = () => {
              return currentSubscription
            }

            this.onSave = selectedPlanId => {
              $uibModalInstance.dismiss()
              const newPlan = this.plans().find(plan => plan.id === selectedPlanId)
              $scope.changePlanFromModal(newPlan)
            }
          },
          controllerAs: '$ctrl',
          keyboard: false,
          backdrop: 'static',
          resolve: {},
        })
      }

      /**
       * Opens the Express plans modal
       */
      $scope.openPlansModal = async () => {
        $uibModal.open({
          template: expressPlansModalTemplate,
          controller($uibModalInstance, config, lang, selectPlan) {
            Object.assign(this, {
              config,
              lang,
              selectPlan,
            })

            this.plans = () => {
              return $scope.membershipPlans
            }

            const currentSubscription = express.userCurrentSubscription?.subscription

            this.currentPlan =
              currentSubscription?.subscription_plan?.polytype === SUBSCRIPTION_TYPES.TRIAL
                ? currentSubscription?.next_subscription_plan
                : currentSubscription?.subscription_plan

            this.updatePlan = plan => {
              $uibModalInstance.dismiss()
              $scope.changePlanFromModal(plan)
            }
          },
          controllerAs: '$ctrl',
          keyboard: false,
          backdrop: 'static',
          resolve: {
            config() {
              return $scope.config
            },
            selectPlan() {
              return express.updateMembershipPlan
            },
          },
        })
      }

      /**
       * Opens the promo plan change modal
       */
      $scope.openPromoPlanChangeModal = async () => {
        $uibModal.open({
          template: expressPromoPlanChangeModalTemplate,
          controller($uibModalInstance) {
            this.plan = () => {
              return $scope.getPromoPlan()
            }

            this.onPlanChange = () => {
              $uibModalInstance.dismiss()
              $scope.changePlanFromModal(this.plan())
            }
          },
          controllerAs: '$ctrl',
          keyboard: false,
          backdrop: 'static',
          resolve: {},
        })
      }

      $scope.nextPlanDescriptionText = () => {
        const nextPlan = $scope.getNextSubscriptionPlan()
        const templateString = nextPlan.promo
          ? config.lang.icExpress.profilePage.nextPlanDescriptionTextPromo
          : config.lang.icExpress.profilePage.nextPlanDescriptionText
        const text = interpolate(templateString, {
          planName: nextPlan.name,
          activationDate: $scope.getNextPlanDate().format('MMMM D, YYYY'),
          price: `${$scope.config.currencySymbol}${nextPlan.price}`,
          fullPrice: `${$scope.config.currencySymbol}${nextPlan.full_price}`,
          termLength: nextPlan.term_length,
        })
        return text
      }

      $scope.showNextPlanSection = () => {
        if (!$scope.isAutoRenew()) {
          return false
        }
        return $scope.getNextSubscriptionPlan().name !== $scope.getCurrentSubscription().name
      }

      $scope.showChangePlanButton = () => {
        return (
          $scope.isAutoRenew() &&
          express.isExpressApplicable() &&
          $scope.currentCreditCard().ext_id &&
          $scope.membershipPlans.length > 0
        )
      }

      $scope.getPromoPlan = () => {
        const promoPlanId = $scope.config.icExpress.profilePage?.promoPlanChange?.promoPlanId
        return $scope.membershipPlans.find(plan => plan.id === promoPlanId)
      }

      $scope.shouldShowPromoPlanChange = () => {
        const isComponentEnabled = $scope.config.icExpress.profilePage.promoPlanChange?.enabled
        return (
          isComponentEnabled &&
          $scope.isAutoRenew() &&
          !$scope.isCoupon() &&
          $scope.getPromoPlan() &&
          $scope.getNextSubscriptionPlan()?.id !== $scope.getPromoPlan()?.id
        )
      }

      $scope.showPaymentInfo = () => {
        return $scope.isAutoRenew()
      }

      $scope.showCouponMessage = () => {
        return $scope.isCoupon() && lang.values.icExpress.couponDurationText
      }

      this.loadInitialData()
    }
  )
