import angular from 'angular'
import { getUrbanAirshipChannelId } from '@app/utils/getUrbanAirshipChannelId'
import { DEVICE_TYPES } from './mobile'

export default angular
  .module('urbanAirServicesModule', [])
  .run(($rootScope, Restangular, config, mobileService, universalLinksService) => {
    function handleDeepLink() {
      window.UAirship.getDeepLink(true, link => {
        if (link) {
          let decodedLink = link
          // Deep links from Urban Airship are URL encoded
          try {
            decodedLink = decodeURIComponent(link)
          } catch (error) {
            decodedLink = link
          }
          universalLinksService.didLaunchAppFromLink({ url: decodedLink })
        }
      })
    }

    function initializeUAirship() {
      if (!window.UAirship) return

      // Wait for the current state change to complete before re-routing
      $rootScope.$on('firstStateChangeSuccess', () => {
        handleDeepLink()
      })
    }

    function resumeUAirship() {
      if (!window.UAirship) return

      window.UAirship.resetBadge()
      initializeUAirship()
    }

    function updateChannelID(channelID) {
      const deviceType = mobileService.getMobileOs() || DEVICE_TYPES.UNKNOWN
      Restangular.one('user_sessions').customPUT({
        device: { channel_id: channelID, device_type: deviceType },
      })
    }

    if (config.urbanAirshipEnabled && mobileService.isMobileApp()) {
      document.addEventListener('deviceready', initializeUAirship, false)
      document.addEventListener('resume', resumeUAirship, false)
      document.addEventListener(
        'urbanairship.deep_link',
        () => {
          if ($rootScope.previousState !== undefined) {
            handleDeepLink()
          }
        },
        false
      )

      $rootScope.$on('auth', (event, action) => {
        if (action === 'userUpdated') {
          getUrbanAirshipChannelId(updateChannelID)
        }
      })
    }
  })
