import angular from 'angular'
import { getAppHistory } from '@app/react/history'

/**
 * Listens to changes to AngularJS $location as well as React history
 * Keeps both instances in sync when change occurs on the other
 */
export default angular
  .module('reactRouterHistoryModule', [])
  .service('reactRouterHistory', function reactRouterHistory($location, $rootScope, analytics) {
    const history = getAppHistory()

    const scope = this

    let skipAngularLocationUpdate = false
    let shouldLogPageview = true

    scope.listen = () => {
      // When the angular location changes, this updates the react router history to keep it in sync
      const removeLocationListener = $rootScope.$on(
        '$locationChangeSuccess',
        (event, newUrl, oldUrl) => {
          if (shouldLogPageview) {
            analytics.onLocationChangeSync(event, newUrl, oldUrl)
            shouldLogPageview = false
          }

          if (history.location.pathname === $location.path()) {
            shouldLogPageview = true
            return
          }

          // Set flag so that the history listener can skip this change
          skipAngularLocationUpdate = true

          history.push($location.url())
        }
      )

      // When the react router history changes we update the angular location to keep it in sync
      let currentPath
      const removeHistoryListener = history.listen(({ pathname, search, hash }) => {
        if (skipAngularLocationUpdate) {
          skipAngularLocationUpdate = false
          return
        }

        /* Setting shouldLogPageview to true when removing history listener at all times, as it 
        breaks mobile tracking with the isSetAlready check below */
        shouldLogPageview = true

        const targetPath = `${pathname}${search}${hash}`
        currentPath = pathname
        // TODO what about query parameters? They usually are treated as different path by browsers
        const isSetAlready = $location.path() === pathname
        if (isSetAlready) {
          return
        }

        $rootScope.$applyAsync(() => {
          /* event.preventDefault will toggle path back for angular-ui-router
          (for example when user is not logged in),
          async action needs help to detect when this happens so we avoid
          flipping back and forth between paths */
          const pathChangedSince = currentPath !== pathname
          if (pathChangedSince) {
            return
          }

          $location.url(targetPath)
        })
      })

      return () => {
        removeLocationListener()
        removeHistoryListener()
      }
    }
  })
