import angular from 'angular'
import { product, user } from '@instacart/enterprise-services'

export default angular
  .module('priceAlertsServicesModule', [])
  .service('priceAlertsService', function priceAlertsService($rootScope, ngPromise) {
    const service = this

    service.deletePriceAlert = productId => product.api.product.removePriceAlertById(productId)

    service.addPriceAlert = productId => product.api.product.createPriceAlertById(productId)

    service.getAllPriceAlerts = () => user.api.user.getPriceAlerts().then(({ items }) => items)

    service.togglePriceAlert = product => {
      if (product.price_alert === null) {
        const addPriceAlert = ngPromise.wrapper(service.addPriceAlert)

        addPriceAlert(product.id).then(priceAlert => {
          $rootScope.$emit('priceAlertChange', {
            productId: product.id,
            priceAlert,
          })
        })
      } else {
        const deletePriceAlert = ngPromise.wrapper(service.deletePriceAlert)

        deletePriceAlert(product.price_alert.id).then(() => {
          $rootScope.$emit('priceAlertChange', { productId: product.id, priceAlert: null })
        })
      }
    }
  })
