import angular from 'angular'
import { CONTENT_ELEMENT } from '@app/angular/constants/elementSelectors'

export default angular
  .module('scrollIntoViewComponentModule', [])
  .directive('scrollIntoView', function ($document, $timeout, utilService) {
    // Set the css selectors for the container that the scroll will happen within and the target element to be scrolled to
    // Scroll container if not set will default to the content view and targetelement the element the directive is called in.

    return function (_scope, element, attrs) {
      $timeout(() => {
        const targetElement = attrs.targetElement
          ? document.querySelector(attrs.targetElement)
          : element[0]
        const scrollContainer = attrs.scrollContainer || CONTENT_ELEMENT
        utilService.scrollIntoView(targetElement, scrollContainer)
      }, 0)
    }
  })
