import angular from 'angular'

export default angular
  .module('stickyComponentModule', [])

  // Directive controlling the sticky header
  // Note: You can listen for the broadcast of the UI events for navStick and navUnStick

  .directive('sticky', $document => {
    const $$document = angular.element(document)

    return function (scope, el, attrs) {
      let elTop
      let stuck
      // eslint-disable-next-line radix
      const offset = attrs.offset ? parseInt(attrs.offset) : 0

      // If sticky-check-callback is defined, it will call the specified function name on the
      // scope, allowing for custom action as the body is scrolled
      const callback =
        typeof scope[attrs.stickyCheckCallback] === 'function' && scope[attrs.stickyCheckCallback]

      function shouldStick() {
        const docTop = $$document[0].documentElement.scrollTop || $$document[0].body.scrollTop
        // If elTop is undefined or 0 the element will never come unstuck, explicitly set it &
        // return false to start the loop
        if (elTop === undefined || elTop === 0) {
          elTop = docTop + el[0].getBoundingClientRect().top
          return false
        }

        if (stuck) {
          return docTop >= elTop - offset
        }
        const newTop = el[0].getBoundingClientRect().top
        elTop = docTop + newTop
        return newTop <= offset
      }

      function stick() {
        if (!stuck) {
          stuck = true
          el.addClass('sticky')
          el.removeClass('un-sticky')

          if (attrs.bodyClass) {
            angular.element($document[0].body).addClass(attrs.bodyClass)
            angular.element($$document[0].body).addClass(`unata-${attrs.bodyClass}`)
          }
        }
      }

      function unstick() {
        if (stuck) {
          stuck = false
          el.removeClass('sticky')
          el.addClass('un-sticky')

          if (attrs.bodyClass) {
            angular.element($document[0].body).removeClass(attrs.bodyClass)
            angular.element($$document[0].body).removeClass(`unata-${attrs.bodyClass}`)
          }
        }
      }

      function check() {
        // Prevent items from remaining stuck if the window is resized
        if (attrs.minWidth && window.innerWidth <= attrs.minWidth) {
          unstick()
        }

        if (!attrs.minWidth || window.innerWidth >= attrs.minWidth) {
          // Allow disabling below a certain screen width
          if (shouldStick()) {
            stick()
          } else {
            unstick()
          }
        }

        if (callback) callback(el)
      }

      scope.$on('$destroy', () => {
        unstick()
        $$document.off('scroll', check)
        $$document.off('wheel', check)
      })

      $$document.on('scroll', check)
      $$document.on('wheel', check)

      check()
    }
  })
