/**
 * Add meta tag with name="description" and content="`${descriptionContent}`"
 *
 * descriptionContent: `A description string for my product`
 * returns: Append to head `<meta name="description" content="A description string for my product"> `
 */

const appendMetaTagDescription = descriptionContent => {
  if (process.env.UNATA_WIDGETS) {
    return
  }

  const descriptionTags = document
    .getElementsByTagName('head')[0]
    .querySelectorAll('meta[name="description"]')
  if (descriptionTags.length > 0) {
    descriptionTags[0].content = descriptionContent
  } else {
    const meta = document.createElement('meta')
    meta.setAttribute('name', 'description')
    meta.setAttribute('content', descriptionContent)
    document.getElementsByTagName('head')[0].appendChild(meta)
  }
}

export default appendMetaTagDescription
