import angular from 'angular'
import * as storage from '@app/utils/storage'
import template from './alert.html'

const ALERT_CLOSE_STORE_KEY = 'alert_closes'

const ALERT_ICON_MAP = {
  'warn-light': 'icon-warning',
  'info-positive': 'icon-info-i',
}

export default angular.module('AlertComponentModule', []).component('alert', {
  transclude: true,
  bindings: {
    /** { string } - The type as defined by ALERT_ICON_MAP above */
    type: '@',

    /** [{ boolean }] - Enables this notice to be closed */
    closeable: '@?',
    /** [{ boolean }] - Set to true to disable the border */
    disableBorder: '@?',
    /** [{ string }] - Custom icon to use */
    icon: '@?',
    /** [{ string }] - Custom css class to add on */
    noticeClass: '@',
    /** [{ function }] - Callback when the alert is closed */
    onClose: '&?',
    /** [{ string }] - Keeps the closed status in localStorage via this ID */
    rememberCloseStatusById: '@?',
    /** [{ boolean }] - Set to true for small text */
    smallText: '@?',
  },
  template,
  controller($q) {
    const storageService = storage.getInstance()

    this.$onInit = () => {
      this.icon = ALERT_ICON_MAP[this.type] || this.icon

      isAlertVisible(this).then(isVisible => (this.isVisible = isVisible))
    }

    this.close = () => {
      this.isVisible = false
      if (this.onClose) this.onClose()

      if (this.rememberCloseStatusById) {
        storageService.setItem(
          ALERT_CLOSE_STORE_KEY,
          JSON.stringify([this.rememberCloseStatusById, ...this.alreadyClosedArray])
        )
      }
    }

    function isAlertVisible(context) {
      if (context.rememberCloseStatusById) {
        return storageService
          .getItem(ALERT_CLOSE_STORE_KEY)
          .then(value => {
            context.alreadyClosedArray = value || []
            const isClosed = context.alreadyClosedArray.includes(context.rememberCloseStatusById)
            if (isClosed && context.onClose) {
              context.onClose()
            }
            return !isClosed
          })
          .catch(() => true)
      }

      return $q.resolve(true)
    }
  },
})
