import _ from 'lodash'
import angular from 'angular'
import tagHelper from '@app/angular/reactComponents/checkbox/tagHelper'

export default angular
  .module('FiltersBarResizeHideModule', [])
  .directive('filtersBarResizeHide', function ($timeout, $window, tagsService) {
    return {
      link: function (scope, element, attrs) {
        const root = element.children()[0]
        const wrapper = angular.element(root).children()[0]
        const toggles = angular.element(wrapper).children()[0]
        const more = angular.element(toggles).children()[0]
        let elWidth = 0

        root.style.visibility = 'hidden'

        // Ideally this would all be more custom with manual insetions in the DOM or pre-calculations of element dimensions
        const calculateFilters = function () {
          scope.$applyAsync(scope => {
            scope.extraFilters = []
            const manipulate = function () {
              _.eachRight(angular.element(toggles).children(), (el, index) => {
                if (index > 0) {
                  // at index(0) we have the "More Filters" dropdown link and it doesn't count as a filter
                  const tag = el.getAttribute('data-tag-name')
                  const label = tagsService.byVal(tag, 'filters')
                  const tagFixes = tagHelper(label)
                  const mergedLabel = { ...label, ...tagFixes }
                  if (
                    el.offsetTop + el.offsetHeight > toggles.offsetTop + toggles.offsetHeight &&
                    !_.includes(scope.extraFilters, mergedLabel)
                  ) {
                    if (mergedLabel) {
                      // prevent adding duplicate tags
                      const index = scope.extraFilters.findIndex(x => x.id === mergedLabel.id)
                      if (index === -1) {
                        scope.extraFilters.push(mergedLabel)
                      }
                    }
                  }
                }
              })
            }

            // The first timeout accounts from when the filters are added in the DOM and some of them are pushed out

            $timeout(() => {
              manipulate()

              // The second timeout is for when the dropdown gets added and then pushes one or more filters out of the way

              $timeout(() => {
                manipulate()
                root.style.visibility = 'visible'
                elWidth = root.offsetWidth
              }, 0)
            }, 0)
          })
        }

        calculateFilters()

        const wrapperWidth = scope.$watch(
          () => angular.element(wrapper)[0].getBoundingClientRect().width,
          (newVal, oldVal) => {
            if (oldVal !== newVal) {
              calculateFilters()
            }
          }
        )

        scope.$on('$destroy', wrapperWidth)
      },
    }
  })
