import angular from 'angular'
import debounce from 'lodash/debounce'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import template from './modifyOrderBanner.html'

export default angular.module('ModifyOrderBannerModule', []).component('modifyOrderBanner', {
  bindings: {},
  template,
  controller(
    $filter,
    $q,
    $rootScope,
    $scope,
    $timeout,
    cartService,
    reservationService,
    lang,
    shoppingContextService,
    mobileService
  ) {
    Object.assign(this, {
      $filter,
      $q,
      $rootScope,
      $scope,
      $timeout,
      cartService,
      reservationService,
      lang,
      mobileService,
      shoppingContextService,
    })

    const setShoppingIntentTitle = () => {
      this.shoppingIntentTitle = shoppingContextService.getShoppingModeTitle()
    }

    $scope.$watch(() => shoppingContextService.getShoppingModeTitle(), setShoppingIntentTitle)

    // This method gets fired upon each render. Once the renders have settled down (i.e. the UI
    // has mounted), and if the isTemporaryModifyExpired is set, we cancel the modification
    this.finishedRender = debounce(() => {
      if ($rootScope.isTemporaryModifyExpired) {
        $rootScope.isTemporaryModifyExpired = false // Ensure not called twice
        cartService.cancelOrderModification()
      }
      // This needs to be 1000 ms as values are not initiated within 100 ms and the `cancelOrderModification` is called always preventing us from reaching checkout
    }, 1000)

    eventEmitter.on(EVENT_TYPES.AUTO_CANCEL_MODIFICATION, () => {
      this.finishedRender()
    })
  },
})
