import angular from 'angular'

import { IC_PLACEMENTS } from '@app/angular/constants/express'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'

export default angular
  .module('RegistrationControllerModule', [])
  .controller(
    'RegistrationController',
    function (
      $rootScope,
      $scope,
      $uibModalInstance,
      config,
      error,
      express,
      importIcAccountService,
      redirectService,
      refocusService,
      registrationService,
      singleView,
      socialSignOnData,
      socialSignOnService,
      subscriptionCode,
      userAnalytics,
      view
    ) {
      $scope.registrationService = registrationService
      $scope.error = error
      $scope.view = view
      $scope.socialSignOnData = socialSignOnData
      $scope.subscriptionCode = subscriptionCode

      $scope.setView = function (view) {
        const previousView = registrationService.view

        if (previousView === 'register') {
          userAnalytics.logUserRegistrationExit()
        }

        if (view === 'register') {
          userAnalytics.logUserRegistrationStart()
        }

        // Update view
        registrationService.view = view
        $scope.view = view
      }

      $scope.showTab = function (tab) {
        if (!singleView) {
          return true
        }
        if (singleView === tab) {
          return true
        }
        return false
      }

      $scope.goToSocialLinkFlow = () => {
        socialSignOnService.goToSocialLinkFlow(socialSignOnData).then(data => {
          // account successfully linked
          if (data.linked) {
            // close register modal since we no longer need to sign up linked user
            $uibModalInstance.close()
          }
        })
      }

      $scope.onRegister = () => {
        $uibModalInstance.close()
        const redirected = redirectService.loadRedirectUrl() !== false
        if (config.icImportAccount) {
          return !redirected
            ? $rootScope
                .reloadState()
                .then(() => importIcAccountService.openModal(LOGIN_CONTEXTS.ON_REGISTRATION))
            : importIcAccountService.openModal(LOGIN_CONTEXTS.ON_REGISTRATION)
        }
        if (!redirected) {
          $rootScope.reloadState()
        }
      }

      $scope.express = express
      $scope.IC_PLACEMENTS = IC_PLACEMENTS

      // Initialize `modal.closing` trigger to refocus invoking element when this modal closes
      refocusService.setRefocusTrigger($scope)
    }
  )
