// This directive can be used in two ways. Either directly if the offer you have
// access to on scope is a full cart offer (ie. offer-tile="offer"), otherwise
// you can provide a product's offer reference, and it will fetch the all of the
// associated offer info (ie. <div offer-tile offer-data="offer"> ).
import angular from 'angular'
import template from './offerTile.html'

export default angular.module('OfferTileModule', []).directive('offerTile', function () {
  return {
    scope: {
      offer: '=?offerTile',
      offerData: '=',
      offerOrigin: '<?',
      location: '@',
      disableCouponClipping: '<?',
    },
    template: template,
    controller: function (
      $rootScope,
      $scope,
      $element,
      $attrs,
      config,
      cartService,
      lang,
      offerAnalytics,
      offerDetailsModalService,
      offersService
    ) {
      $scope.config = config
      $scope.lang = lang
      $scope.offerDetailsModalService = offerDetailsModalService
      $scope.openOfferModal = offer => {
        const offerId = offer.id
        const { offerOrigin } = $scope

        if (!offer.has_details) {
          return
        }

        offersService.getOffer(offerId).then(offer => {
          offerDetailsModalService.open(offer)

          offerAnalytics.viewOffers({
            offers: [offer],
            offerPageMeta: { count: 1 },
            offerOrigin: offerOrigin && Object.assign({ store_product: offerOrigin }, offerOrigin),
            is_modal: true,
          })
        })
      }

      if ($scope.offerData) {
        // offerData is an offer reference with not all of the offer information, so in order to populate the offer information for
        // the tile we need to use the getOfferData function.
        offersService.getOfferData($scope.offerData.id).then(offerProgressData => {
          $scope.offer = offerProgressData
        })
        // We need to listen for the cart being synced so that the offer progress indicator
        //  will be updated when the cart changes.
        const deregisterListener = $scope.$watch(
          () => cartService.lastUpdated,
          newVal => {
            offersService.getOfferData($scope.offerData.id).then(offerProgressData => {
              $scope.offer = offerProgressData
            })
          }
        )
        $scope.$on('$destroy', () => {
          deregisterListener()
        })
      }
    },
  }
})
