import angular from 'angular'
import template from './authorizeNetIntegration.html'

export default angular
  .module('authorizeNetPaymentServiceModule', [])
  .config(paymentGatewayProvider => {
    paymentGatewayProvider.registerGateway({
      name: 'Authorize.net',
      key: 'auth_capture',
      template,
      serviceName: 'authorizeNetPayment',
      newCheckoutCssClass: 'provider-generic_styling',
    })
  })

  .service('authorizeNetPayment', function authorizeNetPayment($q, defaultPayments) {
    const service = this

    service.status = {}

    service.checkout = defaultPayments.checkout
  })
