// A directive to prevent all touchmove events on the specified element.
// Mostly to account for modals on mobile.

import angular from 'angular'

export default angular
  .module('PreventAllScrollingModule', [])
  .directive('preventAllScrolling', $timeout => {
    return {
      link(scope, el) {
        $timeout(() => {
          el[0].addEventListener(
            'touchmove',
            event => {
              event.preventDefault()
            },
            false
          )
        }, 0)
      },
    }
  })
