import angular from 'angular'
import template from './icExpressSignin.html'

class IcExpressSignin {
  constructor(config, lang, utilService) {
    Object.assign(this, {
      config,
      lang,
      utilService,
    })
  }

  $onInit() {
    const { title, subtitle } = this.config.icExpress.placements.signin
    this.title = this.title || title
    this.subtitle = this.subtitle || subtitle
  }
}

export default angular.module('IcExpressSigninComponentModule', []).component('icExpressSignin', {
  bindings: {
    title: '@',
    small: '<',
    subTitle: '@',
    enableRetailerTheme: '<',
  },
  template,
  controller: IcExpressSignin,
})
