import angular from 'angular'
import template from './braintreeIntegration.html'

export default angular
  .module('BraintreePaymentServiceModule', [])
  .config(paymentGatewayProvider => {
    paymentGatewayProvider.registerGateway({
      name: 'Braintree',
      key: 'auth_capture',
      template,
      serviceName: 'braintreePayment',
      newCheckoutCssClass: 'provider-braintree_styling',
    })
  })

  .service(
    'braintreePayment',
    function braintreePayment($q, $rootScope, $timeout, defaultPayments) {
      const service = this

      service.status = { loaded: true }

      service.checkout = defaultPayments.checkout
      service.onAfterCheckoutError = () => {
        $rootScope.reloadState()
      }

      // Called by BraintreeIntegrationController to indicate that payment has been verified
      service.verifiedPayment = () => {}

      service.verifyPayment = () => {
        return new Promise((resolve, reject) => {
          // $timeout ensures that Angular gets to handle the events so that Angular doesn't cry a river of errors
          $timeout(() => {
            const formBrainTree = document.getElementById('formBrainTree')
            if (formBrainTree) {
              service.verifiedPayment = resolve

              // To verify without a button:
              // https://developers.braintreepayments.com/reference/client-reference/javascript/v2/best-practices#bypass-submit-handlers
              const e = document.createEvent('Event')
              e.initEvent('submit', true, true)
              formBrainTree.dispatchEvent(e)
            } else {
              // eslint-disable-next-line prefer-promise-reject-errors
              reject('#formBrainTree is missing!')
            }
          })
        })
      }
    }
  )
