import _ from 'lodash'
import { AUTHORIZE_NET } from '@app/angular/constants/paymentGateway'
import angular from 'angular'

const { FORM_ACTION_FN } = AUTHORIZE_NET
const AUTHORIZE_NEW_CALLBACK = 'authorizeNetCallback'
const ACTIONS = {
  ADD_PAYMENT: 'addPayment',
  EDIT_PAYMENT: 'editPayment',
}
const EVENTS = {
  RESIZE_WINDOW: 'resizeWindow',
  CANCEL: 'cancel',
  SUCCESSFUL_SAVE: 'successfulSave',
}

export default angular
  .module('AuthorizeNetIntegrationControllerModule', [])
  .controller(
    'AuthorizeNetIntegrationController',
    function ($scope, $timeout, Restangular, authorizeNetPayment, config, utilService) {
      const onShowCardForm = $scope.onShowCardForm || $scope.$parent.onShowCardForm
      const checkoutNewCreditCard =
        $scope.checkoutNewCreditCard || $scope.$parent.checkoutNewCreditCard
      const setCreditCard = () => $scope.doSave && $scope.doSave()

      const iframe = document.querySelector('#iframeAuthorizeNet')

      const authCaptureEvent = function (options) {
        const form = document.forms.formAuthorizeNet
        form.action = FORM_ACTION_FN({
          isProd: utilService.isProd(),
          isEdit: options.action === ACTIONS.EDIT_PAYMENT,
        })

        if (form.elements.PaymentProfileId) {
          form.elements.PaymentProfileId.value = options.paymentProfileId
            ? options.paymentProfileId
            : ''
        }

        form.submit()
        iframe.scrollIntoView()
        if (onShowCardForm) {
          onShowCardForm()
        }
      }

      $scope.isIframeVisible = false

      if ($scope.toggleIsShowingAddCard) {
        $scope.toggleIsShowingAddCard(false)
      }

      /**
       * Loads the credit card list
       *
       * @param {boolean} select        - Causes a credit card to be selected as determined by this function
       */
      $scope.getPaymentInstruments = function (select = true) {
        Restangular.one('user')
          .one('payment')
          .get({ payment_type: 'auth_capture' })
          .then(response => {
            const instruments = response.auth_capture.payment_instruments
            const firstInstrument = instruments && instruments.length && instruments[0]
            let newCard = null

            if ($scope.paymentInstruments && instruments) {
              const previousList = $scope.paymentInstruments.map(instrument => instrument.ext_id)
              newCard = instruments.find(instrument => !previousList.includes(instrument.ext_id))
            }

            $scope.paymentInstruments = instruments
            authorizeNetPayment.status.loaded = true

            if ($scope.paymentInstruments.length) {
              if (newCard) {
                $scope.paymentInstrumentId = newCard.ext_id
              } else if (!$scope.paymentInstrumentId) {
                // If nothing chosen, select first one by default
                $scope.paymentInstrumentId = firstInstrument && firstInstrument.ext_id
              } else {
                // Make sure previously choice still exists
                const stillExists = !!_.find($scope.paymentInstruments, {
                  ext_id: $scope.paymentInstrumentId,
                })
                if (!stillExists) {
                  // Default for first one if not
                  $scope.paymentInstrumentId = $scope.paymentInstruments[0].ext_id
                }
              }

              if (select) {
                $timeout(setCreditCard)
              }
            } else {
              // Handle no payment methods existing
              $scope.paymentInstrumentId = null
            }
          })
      }

      $scope.paymentInstrumentId = $scope.payment?.auth_capture?.ext_id
      $scope.getPaymentInstruments() // Fetch initially

      $scope.getAuthToken = function () {
        Restangular.one('cart')
          .one('payment')
          .customPUT({ payment_type: 'auth_capture' })
          .then(response => ($scope.auth_token = response.auth_capture.payer_token))

        // Fetch a new auth token every 15 minutes
        if ($scope.authTokenTimeout) {
          $timeout.cancel($scope.authTokenTimeout)
        }

        $scope.authTokenTimeout = $timeout($scope.getAuthToken, 15 * 60 * 1000)
      }

      $scope.getAuthToken() // Fetch initially

      $scope.authCaptureAddCard = function () {
        $scope.isIframeVisible = true

        if ($scope.toggleIsShowingAddCard) {
          $scope.toggleIsShowingAddCard(true)
        }

        authCaptureEvent({
          action: ACTIONS.ADD_PAYMENT,
          paymentProfileId: 'new',
        })
      }

      $scope.authCaptureEditCard = function (card) {
        authCaptureEvent({
          action: ACTIONS.EDIT_PAYMENT,
          paymentProfileId: card.ext_id,
        })
      }

      // Expose callback used by the authorize.net IFrameCommunicator
      window.addEventListener(
        'message',
        msg => {
          if (msg.data.type === AUTHORIZE_NEW_CALLBACK && msg.data.action) {
            switch (msg.data.action) {
              case EVENTS.RESIZE_WINDOW:
                iframe.width = msg.data.width
                iframe.height = msg.data.height
                $scope.isIframeVisible = true

                if ($scope.toggleIsShowingAddCard) {
                  $scope.toggleIsShowingAddCard(true)
                }

                break

              case EVENTS.CANCEL:
                iframe.width = 0
                iframe.height = 0
                $scope.getPaymentInstruments(false)
                utilService.blurAndHideKeyboard()
                $scope.isIframeVisible = false

                if ($scope.toggleIsShowingAddCard) {
                  $scope.toggleIsShowingAddCard(false)
                }

                break

              case EVENTS.SUCCESSFUL_SAVE:
                iframe.width = 0
                iframe.height = 0
                $scope.getPaymentInstruments()
                utilService.blurAndHideKeyboard()
                $scope.isIframeVisible = false

                if ($scope.toggleIsShowingAddCard) {
                  $scope.toggleIsShowingAddCard(false)
                }

                if (checkoutNewCreditCard) {
                  checkoutNewCreditCard(true)
                }

                break
            }
          }
        },
        false
      )

      // Allow user to proceed once a payment instrument has been selected
      $scope.$watch('paymentInstrumentId', () => {
        $scope.payment.auth_capture = {
          payment_instrument: {
            ext_id: $scope.paymentInstrumentId,
          },
        }
        authorizeNetPayment.status.ready = !!$scope.paymentInstrumentId
      })
    }
  )
