import angular from 'angular'

export default angular
  .module('NotificationComponentModule', [])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.account.notifications', {
      url: '/notifications',
      views: {
        'content@main.account': {
          template: require('./notifications.html'),
          controller: 'NotificationsController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "notificationsController", webpackPrefetch: true */ './notificationsController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.notificationPreferences)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.notificationPreferences,
          },
        ],
      },
    })
  })
