import _ from 'lodash'
// Sets an "invalid" classname to the label of an invalid input
import angular from 'angular'
import template from './configurableProduct.directive.html'

export default angular
  .module('ConfigurableProductModule', [])
  .directive(
    'configurableProduct',
    function (
      $rootScope,
      auth,
      config,
      configurableProductService,
      lang,
      mobileService,
      reservationService
    ) {
      return {
        restrict: 'EA',
        scope: {
          configType: '@',
          product: '=',
          updateConfigTotalPrice: '=',
          location: '@',
          locationValue: '<',
        },
        template: template,
        link: function (scope, elem, attrs) {
          const initStep = 3
          const initStepMultiple = 5
          const limitStep = 20
          const previousSelect = {}
          let configsPrice = 0
          const configBasePrice = scope.product.sale_price || scope.product.base_price

          scope.lang = lang
          scope.config = config
          scope.auth = auth
          scope.allSelectedConfigurations = []
          scope.selectedItems = {}
          scope.selectedMap = {}
          scope.uploaded = { file: null }
          scope.configurableProductService = configurableProductService
          scope.mobileService = mobileService

          // Set base option to top of options if present
          if (scope.product.product_config && scope.product.product_config.has_pricing) {
            scope.product.product_config.options = _.sortBy(
              scope.product.product_config.options,
              option => {
                return option.type === 'base_selection' ? 0 : 1
              }
            )
          }

          // Hijacks radio functionality to allow deselect if currently selected for optional params
          // Debated using checkbox with limit checking, but it would have amounted to the same type of logic
          // But with more overhead in data manipulation between array vs object for single vs multi param
          scope.selectSingleParamItem = function (option, item) {
            if (!option.mandatory) {
              if (_.isEqual(previousSelect[option.id], item)) {
                delete option.selected
                delete previousSelect[option.id]
              } else {
                previousSelect[option.id] = angular.copy(item)
              }
            }

            scope.consolidateSelectedOptions()
          }

          // See more button
          scope.limit = _.map(scope.product.product_config.options, () => {
            return initStep
          })

          scope.multipleLimit = _.map(scope.product.product_config.options, () => {
            return initStepMultiple
          })

          // Ensures that limitTo for "See More" button is not occluding a single option. If only a single option is hidden, show it
          scope.limitToMoreThanOne = function (optionLength, optionLimitByIndex) {
            return optionLength - optionLimitByIndex > 1 ? optionLimitByIndex : optionLength
          }

          scope.incrementOptions = function (index) {
            scope.limit[index] += limitStep
          }

          scope.incrementOptionsMultiple = function (index) {
            scope.multipleLimit[index] += limitStep
          }

          scope.consolidateSelectedOptions = function () {
            const extractSelectedConfigurations = []
            configsPrice = 0

            _.each(scope.product.product_config.options, option => {
              // Checks that all options are valid, add `invalid` property if invalid
              configurableProductService.validateSingle(option)
              if (option.type === 'single_selection' && option.selected) {
                extractSelectedConfigurations.push({
                  id: option.id,
                  option_name: option.name,
                  item: {
                    id: option.selected.id,
                    name: option.selected.name,
                    price: option.selected.price,
                  },
                })

                if (option.selected.price) {
                  configsPrice += option.selected.price
                }
              } else if (option.type === 'base_selection' && option.selected) {
                extractSelectedConfigurations.push({
                  id: option.id,
                  option_name: option.name,
                  base: {
                    id: option.selected.id,
                    name: option.selected.name,
                    price: option.selected.price,
                  },
                })

                if (option.selected.price) {
                  configsPrice += option.selected.price
                }
              } else if (option.type === 'multiple_selection' && option.selected) {
                extractSelectedConfigurations.push({
                  id: option.id,
                  option_name: option.name,
                  items: option.selected.items,
                })

                _.each(option.selected.items, item => {
                  if (item.price) {
                    configsPrice += item.price
                  }
                })
              } else if (
                (option.type === 'text' || option.type === 'text_field') &&
                option.selected
              ) {
                extractSelectedConfigurations.push({
                  id: option.id,
                  text: option.selected,
                  option_name: option.name,
                  price: option.price,
                })

                if (option.price) {
                  configsPrice += option.price
                }
              } else if (
                option.type === 'file' &&
                scope.uploaded.file &&
                scope.uploaded.file.success === true
              ) {
                extractSelectedConfigurations.push({
                  id: option.id,
                  option_name: option.name,
                  price: option.price,
                  file: {
                    id: scope.uploaded.file.id,
                    name: scope.uploaded.file.name,
                    url: scope.uploaded.file.url,
                  },
                })

                if (option.price) {
                  configsPrice += option.price
                }
              }
            })

            scope.allSelectedConfigurations = extractSelectedConfigurations
            $rootScope.$broadcast('configurableProduct', scope.allSelectedConfigurations)
            scope.totalPrice = configBasePrice + configsPrice
            scope.updateConfigTotalPrice = scope.totalPrice
          }

          scope.selectMultiParamItem = function (option, item, productOptionIndex) {
            const itemIndex = _.findIndex(
              scope.selectedItems[option.id].items,
              selectedItemsItem => {
                return selectedItemsItem.id == item.id
              }
            )

            if (itemIndex !== -1) {
              scope.selectedItems[option.id].items.splice(itemIndex, 1)
            } else {
              scope.selectedItems[option.id].items.push({
                id: item.id,
                name: item.name,
                price: item.price,
              })
            }

            if (scope.selectedItems[option.id].items.length > 0) {
              scope.selectedItems[option.id].items = _.sortBy(
                scope.selectedItems[option.id].items,
                'id'
              )
              option.selected = angular.copy(scope.selectedItems[option.id])
            } else {
              delete option.selected
            }

            scope.consolidateSelectedOptions()
          }

          // Initializes default selections
          _.each(scope.product.product_config.options, option => {
            if (option.type === 'multiple_selection') {
              scope.selectedItems[option.id] = {
                id: option.id,
                items: [],
              }
            }

            _.each(option.items, (optionItem, index) => {
              if (optionItem.default === true) {
                option.selected = optionItem

                // Moves default item to beginning of array so it is not hidden by "See More"
                option.items.splice(index, 1)
                option.items.unshift(optionItem)

                if (!option.mandatory) {
                  previousSelect[option.id] = angular.copy(optionItem)
                }

                if (option.type === 'multiple_selection') {
                  //Leverage the function that the template uses to handle selecting a multi param item, for the default
                  scope.selectMultiParamItem(option, optionItem)
                  //HTML template binds the multiple selection inputs to the selectedMap object for UI selection indicator, so it needs to be initialized
                  scope.selectedMap[`${option.id}-${optionItem.id}`] = true
                }
              }
            })
          })

          // The prices that are shown in the base selection option are a combination of the config base price and the base selection price
          // for the corresponding item. For instance, if the configBasePrice was $5.00, the base selction prices might be: small 0, medium 3, and large 5
          // to give you the displayed options of $5 small, $8 medium and $10 large
          scope.displayBaseSelectionTotalPrice = baseSelectionPrice => {
            return configBasePrice + baseSelectionPrice
          }
          scope.$watch('uploaded.file', (newVal, oldVal) => {
            if (!_.isEqual(newVal, oldVal)) {
              scope.consolidateSelectedOptions()
            }
          })

          scope.consolidateSelectedOptions()
        },
      }
    }
  )
