import angular from 'angular'
import { auth as authService } from '@instacart/enterprise-services'
import { captureException } from '@sentry/browser'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { OneCartActions } from '@app/services/oneCart/types'
import isMobileApp from '@app/utils/isMobileApp'

export default angular
  .module('OrderIcRedirectControllerModule', [])
  .controller(
    'OrderIcRedirectController',
    function (
      $location,
      $rootScope,
      $scope,
      $state,
      $stateParams,
      auth,
      config,
      lang,
      localStorageService,
      oneCartService,
      registrationService
    ) {
      const queryParams = {}
      if ($stateParams.add_to_order) {
        queryParams.add_to_order = ''
      }
      $rootScope.isAppLoading = true

      let authFunction = authService.api.auth.getOneCartOrderRedirect
      let action = OneCartActions.ORDER_STATUS

      // this means its a certified delivery link not a order status link
      if ($stateParams.id.indexOf('cd') === 0) {
        authFunction = authService.api.auth.getCertifiedDeliveryRedirect
        action = OneCartActions.CERTIFIED_DELIVERY
      }

      // $stateParams.id is the IC order id(including the date) for example 03163808646437130581
      authFunction($stateParams.id, queryParams)
        .then(resp => {
          return oneCartService
            .redirectToPBI(resp.location, resp.auth_token, { action })
            .then(() => {
              // This redirect is required for mobile so users don't see the empty view of this controller
              if (isMobileApp()) {
                $state.go('main.account.orderHistory')
              }
            })
        })
        .catch(e => {
          if (e.error === HTTP_ERRORS.PII_UNAUTHORIZED) {
            // If PII has timed out
            localStorage.setItem('icOrderId', $stateParams.id)
            localStorageService.deprecatedSet('icOrderQuery', queryParams)

            registrationService.promptLogin(
              $state.current.name,
              $state.params,
              '',
              '',
              lang.values.loginToContinue,
              LOGIN_CONTEXTS.REDIRECT_ONECART_ORDER_STATUS
            )
            return
          }

          captureException(e)
          $rootScope.isAppLoading = false
          $location.url('/account/order-history')
        })
    }
  )
