import { ShoppingListItem } from '@app/types/shoppingList'
import EncryptorFactory from '@app/utils/rx/encryptorFactory'
import { getRxDetails } from './api'
import { DecryptedRxMessage, GetRxDetailsResponse } from './types'

export const parseDetails = (details: DecryptedRxMessage) => {
  const parsedDetail: Record<string, string> = {}
  Object.keys(details)?.forEach((key: string) => {
    parsedDetail[key] = details[key].name
  })

  return parsedDetail
}

export const obfuscationHelper = async (rx_items: ShoppingListItem[]) => {
  const Encryptor = EncryptorFactory()
  const messageEncryptor = new Encryptor()

  const payload = {
    k: messageEncryptor.publicKey,
    rx_item_id_mapping: rx_items.map(rx_item => {
      return {
        ic_item_id: rx_item.store_product.id,
        prescription_id: rx_item.ext_data?.prescription_id ?? '',
      }
    }),
  }

  const rx_details: GetRxDetailsResponse = await getRxDetails(payload)

  if (!rx_details) {
    return {}
  }

  const { message, external_public_key } = rx_details

  if (message && external_public_key) {
    messageEncryptor.setExternalKey(external_public_key)
    const jsonMessage = messageEncryptor.decrypt(message)
    const details: DecryptedRxMessage = JSON.parse(jsonMessage)
    return parseDetails(details)
  }

  return {}
}
