import angular from 'angular'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'

import { SHOPPING_MODES } from '@app/angular/constants/context'

export default angular
  .module('TimeslotSelectorControllerComponentModule', [])
  .controller(
    'TimeslotSelectorController',
    function (
      $q,
      $rootScope,
      $scope,
      $state,
      auth,
      cartService,
      config,
      errorModalService,
      instacartCustomerLinkModalService,
      lang,
      mobileService,
      modalOptions,
      registrationService,
      reservationService,
      shoppingContextService,
      shoppingSelectorService,
      timeslotService
    ) {
      $scope.config = config
      $scope.lang = lang
      $scope.shopContextIntent = auth.getContext().intent
      $scope.shopContextMode = auth.getContext().shopping_mode
      $scope.selectedTimeslot = false
      $scope.reservationService = reservationService
      $scope.mobileService = mobileService
      $scope.$rootScope = $rootScope

      $scope.timeslotsAreSelectable =
        !timeslotService.areTimeslotsSelectableAtCheckoutOnly() ||
        $state.current.name === 'main.checkout.reservation.confirmation'

      $scope.processComplete = function () {
        $scope.selectedTimeslot = true
        // call $dismiss rather than $close to force a $rootScope.reloadState();
        $scope.$dismiss()
      }

      $scope.selectLater = function () {
        $scope.$dismiss()
      }

      $scope.onTimeslotError = function (response) {
        const currentState = $state.current.name
        const { error } = response.data || {}

        switch (error) {
          // Account not linked
          case HTTP_ERRORS.EXTERNAL_AUTH_ERROR:
            return (
              config.instacart.enableAccountLinking &&
              instacartCustomerLinkModalService
                .openModal()
                .then(() => shoppingSelectorService.open('timeslot'))
                .catch(err => {
                  return $q
                    .resolve($state.includes('main.checkout') && $state.go('main.checkout.cart'))
                    .then(() => {
                      $scope.$dismiss()
                      throw err
                    })
                })
            )

          // User logged out or token timed out
          case HTTP_ERRORS.SESSION_EXPIRED_ERROR:
          case HTTP_ERRORS.PII_UNAUTHORIZED:
          case HTTP_ERRORS.MISSING_CART:
            return errorModalService
              .open(lang.values.expiredTokenErrorMessage, 'OK', null)
              .then(() =>
                registrationService.promptLogin(
                  currentState,
                  '',
                  '',
                  '',
                  lang.values.loginToContinue
                )
              )
              .then(() => shoppingSelectorService.open('timeslot'))

          // All other errors.
          default:
            throw response
        }
      }

      $scope.shoppingContextText = shoppingContextService.getShoppingModeTitle(
        $scope.shopContextIntent
      )

      $scope.resetSelectedTimeslot = function () {
        $scope.selectedTimeslot = false
      }

      $scope.isSelectLaterLinkVisible = modalOptions.isTimeslotSelectLaterLinkVisible()
      $scope.getTitle = () =>
        $scope.timeslotsAreSelectable
          ? $scope.lang.values.selectTimeSlotNoDate
          : $scope.lang.values.selectTimePreviewTitle
      $scope.getSubTitle = () =>
        $scope.shopContextIntent === SHOPPING_MODES.DELIVERY
          ? $scope.config.shoppingSelector.delivery.content
          : $scope.config.shoppingSelector.pickup.content
    }
  )
