import angular from 'angular'
import Hammer from 'hammerjs'
import { IC_PLACEMENTS } from '@app/angular/constants/express'
import { hasBannerImage } from '@app/utils/banners'
import { isReactDemo } from '@app/utils/isReactDemo'

export default angular
  .module('SideNavControllerModule', [])
  .controller(
    'sideNavController',
    function (
      $document,
      $element,
      $injector,
      $scope,
      $state,
      $timeout,
      accountMenuService,
      auth,
      config,
      express,
      lang,
      loyaltyInfoService,
      mobileNavService,
      reservationService,
      shoppingSelectorService,
      unataCommService,
      utilService,
      wordpressRestApiService
    ) {
      $scope.reservationService = reservationService
      const accountMenuItems = accountMenuService.getAccountMenuItems()
      $scope.accountMenuItems = config.icExpress.hideExpressLinkInSideNavAccountSection
        ? accountMenuItems.filter(item => item.id !== 'account-menu-main-account-icExpress')
        : accountMenuItems
      $scope.utilService = utilService
      $scope.mobileNavService = mobileNavService
      $scope.config = config
      $scope.shoppingSelectorService = shoppingSelectorService
      $scope.auth = auth
      // For one time binding to work value needs to be undefined. If we fail to load wordpress sidenav
      // we will resort to platform sidenav.
      $scope.showPlatformSideNav =
        $scope.config.dotComModule.showSideMenu &&
        config.dotComModule.mobileNav?.wordpressMenuItems?.length > 0
          ? undefined
          : true
      $scope.hasBannerImage = hasBannerImage()
      $scope.isExpressMenuVisible = () =>
        config.icExpress.enabled &&
        auth.user?.ic_linked &&
        express.isPlacementConfigEnabled('userDropdown')
      $scope.showSocialLinks =
        config.dotComModule.mobileNav.showSocialIcons &&
        Object.keys(config.footerSocialLinks).length

      // Checks if gigya is enabled. We cannot inject this on controller instantiation since the existence of the service
      // is conditional, based on whether or not gigya has been included in `app/angular/optionalComponents`
      const gigyaService =
        config.gigya && config.gigya.enabled ? $injector.get('gigyaService') : null
      $scope.go = function (sref, params, event) {
        event.preventDefault()
        // remove check for params is same, since all links params are null
        if ($state.is(sref)) {
          // do not use utilService.stateIs - it doesn't work because it ignores null/undefined params
          mobileNavService.hideSlideOutMenu()
          $scope.$parent.$parent?.setSearchingTo(false)
        } else {
          $timeout(() => {
            $state.go(sref, params)
          }, 150)
        }
      }

      // Gigya password reset
      $scope.gigyaChangePassword = () => gigyaService.gigyaChangePassword()

      $scope.logout = function () {
        auth.logout()
      }

      // This triggers when a user clicks the menu icon, safely resetting the styles.
      $scope.$watch('mobileNavService.slideOutMenuIsVisible', (newVal, oldVal) => {
        if (newVal !== oldVal) {
          if (newVal) {
            menuEl.style.transform = null
          } else {
            menuEl.style.transform = 'translate(-100%, 0)'
          }
        }
      })

      // there is a 'stateChangeStart' event listener in mobileNavController, which is only loaded in the Angular header,
      // but not the React header since we moved the logics to react.
      // Move the stateChangeStart event listener here to make sure the slide-out menu is closed in React as well
      $scope.$on('$stateChangeStart', () => {
        mobileNavService.hideSlideOutMenu()
        if (isReactDemo() || config.react.enableMobileHeader) {
          // Resembling the logics in the `stateChangeStart` listener of `mobileNavController.js`,
          // resetMobileSearchingState() dispatches a redux action to reset `isSearching` and `searchHidden` state in the mobile search bar.
          mobileNavService.resetMobileSearchingState()
        }
      })

      const dragging = false
      const slideOutEl = $element[0].querySelector('.slide-out-menu')
      var menuEl = $element[0].querySelector('.mobile')
      const contentEl = $element[0].querySelector('.nav-content')
      const menuWidth = menuEl.offsetWidth

      const ham = new Hammer(slideOutEl, {
        inputClass: Hammer.TouchInput,
        drag_block_horizontal: true,
        drag_lock_min_distance: 20,
        hold: false,
        release: true,
        swipe: true,
        tap: false,
        touch: true,
        transform: false,
      })

      const contentHam = new Hammer(contentEl)

      // The deltaX coming from Hammer is currently pretty buggy ( causing the menu
      // to jump around on things like panup events ).
      // TODO: Revisit this UI functionality later.
      //
      // hamNav.on('panleft panright', function(e) {
      //   console.log(e.deltaX);
      //   if (e.deltaX <= 0) {
      //     menuEl.style.transform = 'translate(' + e.deltaX + 'px, 0)';
      //   }
      // });

      // Close the menu if the user swipes to the left over the slide-out-menu element
      ham.on('panend', clearSideMenu)
      // Do the same if the user is swiping over the content block itself (for some mobile browsers)
      contentHam.on('panend', clearSideMenu)

      // We want to close the menu if a user swipes to the left
      function clearSideMenu(e) {
        // If the user has swiped more than 50px to the left, transition the
        // menu off to the side & clear the styles ($timeout isn't firing
        // on all versions of Android so we're using $evalAsync here to make
        // sure the animation is digested properly, kinda weird.)
        if (e.deltaX <= -50) {
          mobileNavService.hideSlideOutMenu()
          $scope.$evalAsync(() => {
            menuEl.style.transform = 'translate(-100%, 0)'
          })
        } else {
          menuEl.style.transform = 'translate(0px, 0px)'
        }
      }

      $scope.instoreContextDetail = function () {
        if (lang.values.contextPersonalization.instore && auth.user.first_name) {
          return lang.template(lang.values.contextPersonalization.instore.loggedIn, {
            userName: auth.user.first_name,
          })
        } else if (lang.values.contextPersonalization.instore && !auth.user.first_name) {
          return lang.values.contextPersonalization.instore.loggedOut
        } else {
          return config.shoppingSelector.instore.title
        }
      }

      $scope.closeMenu = () => mobileNavService.hideSlideOutMenu()
      $scope.$on('sidenav:fallback', () => {
        $timeout(() => {
          $scope.showPlatformSideNav = true
        })
      })

      unataCommService.listen(unataCommService.EventType.TOGGLEMOBILESIDENAV, event => {
        if (event.data.payload && event.data.payload.showMobileSideNav !== undefined) {
          $scope.$apply(() => {
            mobileNavService.setSlideOutMenu(event.data.payload.showMobileSideNav)
          })
        }
      })

      /**
       * Determines if timeslot should be shown in side nav menu
       * Shows if:
       * 1. A reservation is present
       * 2. OR is configured to show reservations at all times
       *
       * @returns {boolean}
       */
      $scope.shouldShowTimeslot = () => {
        return (
          reservationService.displayReservation ||
          !(
            (config.hideTimeslotReservationFromTrialUsers && auth.user.status === 'trial') ||
            config.shoppingContextProcess[auth.getContext().intent]?.hideTimeslot
          )
        )
      }
    }
  )
