import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'

export default angular
  .module('ConfigurableProductServiceModule', [])
  .service('configurableProductService', function configurableProductService() {
    const service = this

    // Finds default parameters for single param configs
    service.findDefault = optionItems => optionItems.find(optionItem => optionItem.default === true)

    // Maps API `product_config` data structure into necessary structure for cart to do comparison for duplicates
    // If no inboundConfiguration is defined, looks for default value based on `product_config`
    service.formatConfigForCart = function (product, inboundConfiguration) {
      let formattedConfiguration
      let selectedConfiguration

      // single param inboundConfigurations are objects, but it needs to be a formatted array of an object that includes the id of the parent option
      if (product.product_config && product.product_config.config_type === 'prep_style') {
        if (inboundConfiguration) {
          selectedConfiguration =
            Array.isArray(inboundConfiguration) && inboundConfiguration.length > 0
              ? inboundConfiguration[0].base || inboundConfiguration[0].item
              : inboundConfiguration
        }

        if (product.product_config.options.length > 0) {
          // Get default option
          selectedConfiguration =
            selectedConfiguration || service.findDefault(product.product_config.options[0].items)
        } else {
          // Return null when no inboundConfiguration or config options
          return null
        }

        const selectionType =
          product.product_config.options[0].type === 'base_selection' ? 'base' : 'item'

        formattedConfiguration = [
          {
            id: product.product_config.options[0].id,
            option_name: product.product_config.options[0].name,
            name:
              Array.isArray(selectedConfiguration) && selectedConfiguration.length > 0
                ? selectedConfiguration[0].name
                : selectedConfiguration?.name,
            status: product.product_config.options[0].status,
          },
        ]

        formattedConfiguration[0][selectionType] = {
          id: selectedConfiguration.id,
          name: selectedConfiguration.name,
          status: selectedConfiguration.status,
          ...(selectedConfiguration?.default && {
            default: selectedConfiguration?.default,
          }),
        }

        return formattedConfiguration
      }
      if (inboundConfiguration) {
        // return inboundConfiguration as-is if not prep_style
        return inboundConfiguration.options ? inboundConfiguration.options : inboundConfiguration
      }
      // return null if no inboundConfiguration
      return null
    }

    const parseOutNilProps = obj => JSON.parse(angular.toJson(_.omitBy(obj, _.isNil)))
    const sanitizeConfig = obj => _.omit(parseOutNilProps(obj), 'status')

    // Ensures that all $$hashkeys from Angular and `null` values are stripped. Forces config to be an array (requirement for areProductConfigsAndOptionsEqual utility method)
    service.formatConfigForComparison = function (inboundConfiguration) {
      // _.map ensures inboundConfiguration is never null, which is important because we must always return an array (even empty)
      return _.map(inboundConfiguration, itemConfig => {
        if (itemConfig.item) {
          itemConfig.item = sanitizeConfig(itemConfig.item)
        }
        if (itemConfig.base) {
          itemConfig.base = sanitizeConfig(itemConfig.base)
        }
        return sanitizeConfig(itemConfig)
      })
    }

    // Validates that configurables do not exceed maximum allowable values
    service.validateSingle = function (option) {
      if (option.mandatory === true && !_.has(option, 'selected')) {
        option.invalid = {
          name: option.name,
          reason: 'Required',
        }
        return false
      }
      delete option.invalid

      if (
        option.type === 'multiple_selection' &&
        option.selected &&
        option.maximum_selection &&
        option.selected.items &&
        option.selected.items.length > option.maximum_selection
      ) {
        option.invalid = {
          name: option.name,
          reason: `Exceeded maximum: ${option.maximum_selection}`,
        }
        return false
      }
      delete option.invalid

      if (
        (option.type === 'text' || option.type === 'text_field') &&
        option.selected &&
        option.max_length &&
        option.selected.length > option.max_length
      ) {
        option.invalid = {
          name: option.name,
          reason: `Exceeded character limit: ${option.max_length}`,
        }
        return false
      }
      delete option.invalid
    }

    // Checks all options to see if anything is invalid
    service.validateAll = function (options) {
      const invalidOptionsString = []

      _.each(options, option => {
        if (option.invalid) {
          invalidOptionsString.push(`${option.invalid.name} (${option.invalid.reason})`)
        }
      })

      if (invalidOptionsString.length > 0) {
        return invalidOptionsString.join(', ')
      }
      return true
    }

    // Creates a comma-delimited string for all configurations
    service.configsString = function (product, allowHTML) {
      const configsArray = []
      const { options: storeProdOptions } =
        (product.store_product && product.store_product.product_config) || {}
      const { options: storeOptions } = product.product_config || {}

      _.each(storeProdOptions, availableOption => {
        _.each(storeOptions, selectedOption => {
          if (availableOption.id === selectedOption.id) {
            if (availableOption.type === 'single_selection') {
              configsArray.push(`${availableOption.name}: ${selectedOption.item.name}`)
            } else if (availableOption.type === 'base_selection') {
              configsArray.push(`${availableOption.name}: ${selectedOption.base.name}`)
            } else if (availableOption.type === 'text' || availableOption.type === 'text_field') {
              // eslint-disable-next-line no-useless-concat
              configsArray.push(`${availableOption.name}: ` + `"${selectedOption.text}"`)
            } else if (availableOption.type === 'multiple_selection') {
              let multipleString
              const multipleItemArray = []

              multipleString = `${availableOption.name}: `

              _.each(selectedOption.items, item => {
                multipleItemArray.push(item.name)
              })

              configsArray.push((multipleString += multipleItemArray.join(', ')))
            } else if (availableOption.type === 'file') {
              if (allowHTML) {
                configsArray.push(
                  `${availableOption.name}: ` +
                    `<light-box image-url="'${selectedOption.file.url}'" image-name="'${selectedOption.file.name}'"></light-box>`
                )
              } else {
                configsArray.push(`${availableOption.name}: ${selectedOption.file.name}`)
              }
            }
          }
        })
      })

      return configsArray.join('\n')
    }

    service.areProductConfigsAndOptionsEqual = (x, y) => {
      if (x.constructor !== Array || y.constructor !== Array) {
        return false
      }

      const smallerArray = x.length > y.length ? y : x
      const largerArray = x.length > y.length ? x : y

      const cmp = (a, b) => {
        // Rely on lodash equality behaviour in the default case
        if (_.isEqual(a, b)) {
          return true
        }

        // Fall back to comparing the option and option item IDs. This
        // is somewhat fragile, but currently necessary for options with
        // the "custom" type.
        if (a.items && b.items) {
          const aItemIDs = new Set(a.items.map(i => i.id))
          const bItemIDs = new Set(b.items.map(i => i.id))

          // Ensure that the selected option items match between the two products,
          // ignoring the order they're provided in.
          return (
            a.id === b.id &&
            aItemIDs.size === bItemIDs.size &&
            [...aItemIDs].every(itemID => bItemIDs.has(itemID))
          )
        }
        if (a.item && b.item) {
          // option_type SINGLE
          return a.id === b.id && a.item.id === b.item.id
        }

        return false
      }

      const arrayDiff = fp.flow(fp.differenceWith(smallerArray, cmp), fp.isEmpty)(largerArray)

      return arrayDiff.length > 0
    }

    // Checks if configurations are equal, both params should be `options` array
    service.isConfigEqual = function (existingConfiguration, inboundConfiguration) {
      const sanitizedExistingConfiguration = service.formatConfigForComparison(
        existingConfiguration
      )
      const sanitizedInboundConfiguration = service.formatConfigForComparison(inboundConfiguration)
      return service.areProductConfigsAndOptionsEqual(
        sanitizedExistingConfiguration,
        sanitizedInboundConfiguration
      )
    }

    // Creates a unique config string for a particular "to-be-purchased" (i.e. in the cart/list) configurable product
    service.generateConfigIdString = product => {
      const storeProductConfigData =
        (product && product.store_product && product.store_product.product_config) || null
      const selectedProductConfig = (product && product.product_config) || null

      if (storeProductConfigData && selectedProductConfig) {
        // Each option is delimited by a semi-colon
        return selectedProductConfig.options
          .map(selectedOption => {
            const optionMetadata = _.find(storeProductConfigData.options, { id: selectedOption.id })

            // If the status if false for the option or item (i.e. "optionMetadata") then many of the informational fields will be null
            // Hence we just return empty string to mitigate printing out null as a string!
            if (
              !optionMetadata ||
              selectedOption.status === false ||
              optionMetadata.status === false
            ) {
              return null
            }

            switch (optionMetadata.type) {
              case 'single_selection':
                return `${selectedOption.item.id}`
              case 'base_selection':
                return `${selectedOption.base.id}`
              case 'multiple_selection':
                return selectedOption.items.map(item => item.id).join(',')
              case 'text': //  (Fall through)
              case 'text_field':
                return `${selectedOption.text}`
              default:
                return null
            }
          })
          .filter(id => id !== null)
          .join(';')
      }
      // Return an empty string if there is no product_config
      return ''
    }
  })
