import angular from 'angular'
import { USER_PAYMENT_ACTION } from '@app/angular/services/modules/analytics/analyticsConstants'
import { validateGiftCardConfigs, validateGiftCard } from './util/validation'

// eslint-disable-next-line angular/controller-name
export default angular
  .module('GiftCardModalControllerModule', [])
  .controller(
    'giftCardModalController',
    function (
      $scope,
      checkoutAnalytics,
      config,
      errorModalService,
      giftCardOptions,
      lang,
      mobileService
    ) {
      validateGiftCardConfigs(config)

      $scope.error = null
      $scope.isMobile = mobileService.isMobile

      if (giftCardOptions?.onClose) {
        $scope.$on('modal.closing', () => giftCardOptions.onClose())
      }

      $scope.submitAddGiftCard = function () {
        const { payment } = giftCardOptions
        const { amount, card_number } = $scope.giftCard || {}

        const { isValid, message } = validateGiftCard(config, $scope.giftCard)
        if (!isValid) return errorModalService.open(message, lang.values.ok)
        if (!payment) return errorModalService.open(lang.values.defaultErrorMessage, lang.values.ok)

        // Add new gift card, push to server, then switch back to the old gift cards. Update to new upon success from server.
        const giftCardsClone = angular.copy(payment.gift_cards)
        $scope.isLoading = true
        payment.gift_cards.push({ amount, card_number })
        payment
          .put()
          .then(response => {
            payment.gift_cards = response.gift_cards // Replace the local cards list with the response version
            $scope.$dismiss()
          })
          .then(() =>
            checkoutAnalytics.addPaymentOption({
              payment,
              success: true,
              user_action: USER_PAYMENT_ACTION.GIFT_CARD_CHANGE,
            })
          )
          .catch(err => {
            errorModalService.open(err.data?.message || err.message, 'OK')
            $scope.isLoading = false
            checkoutAnalytics.addPaymentOption({
              payment,
              success: false,
              user_action: USER_PAYMENT_ACTION.GIFT_CARD_CHANGE,
            })
          })

        payment.gift_cards = giftCardsClone
      }
    }
  )
