import angular from 'angular'

export default angular.module('HelpComponentModule', []).directive('help', function () {
  return {
    scope: {
      descriptionId: '@',
      help: '=',
    },
    transclude: true,
    template:
      '<div class="help-content" ng-bind-html="help" id="{{descriptionId}}" role="tooltip"></div>',
    link: (scope, el) => {
      // Add keyboard focus for accessibility
      el.on('keyup focus', event => {
        if (event.which === 9) {
          el[0].focus()
        }
      })
    },
  }
})
