import React, { memo } from 'react'
import { createStyles, utils } from '@instacart/cocktail'
import PropTypes from 'prop-types'
import { SUBSCRIPTION_PATHS } from './constants'
import Footer from './Footer'
import Checkout from './Checkout'
import SubscriptionMarketing from './steps/SubscriptionMarketing'
import SubscriptionSelection from './steps/SubscriptionSelection'
import SubscriptionPayment from './steps/SubscriptionPayment'
import SubscriptionSuccess from './steps/SubscriptionSuccess'

export const SUBSCRIPTION_STEPS = [
  {
    key: 'marketing',
    component: SubscriptionMarketing,
    isValid: () => true,
    // The following is required to tie in the steps with AngularJS UI router routes
    urlName: '/info',
    data: {
      requireRegistered: false,
    },
  },

  {
    key: 'select',
    component: SubscriptionSelection,
    isValid: () => true,
    // The following is required to tie in the steps with AngularJS UI router routes
    urlName: '/select',
    data: {
      requireRegistered: true,
    },
  },

  {
    key: 'payment',
    component: SubscriptionPayment,
    isValid: param => {
      return !!param.currentParams?.selectedSubscription?.id
    },
    // The following is required to tie in the steps with AngularJS UI router routes
    urlName: '/payment',
    data: {
      requireRegistered: true,
    },
  },

  {
    key: 'success',
    component: SubscriptionSuccess,
    isValid: () => true,
    // The following is required to tie in the steps with AngularJS UI router routes
    urlName: '/purchased',
    data: {
      requireRegistered: true,
    },
  },
]

const useStyles = createStyles(
  tokens => ({
    root: {
      display: 'flex',
      width: '100%',
      backgroundColor: tokens.color.neutral.grey1,
      justifyContent: 'center',
      padding: 0,
      [utils.mediaQuery.minWidth(tokens.breakpoint.small)]: {
        padding: tokens.size.spacing.xxLarge,
      },
    },
    container: {
      maxWidth: tokens.breakpoint.large,
      borderRadius: tokens.border.radius.regular,
      backgroundColor: tokens.color.neutral.white,
    },
    footer: {
      marginTop: tokens.size.spacing.xxLarge,
    },
  }),
  { displayName: 'SubscriptionCheckout' }
)

const SubscriptionCheckout = props => {
  const styles = useStyles(props)
  const {
    config,
    i18n,
    styles: originalStyles,
    services,
    isGiftable,
    isPurchasable,
    ...restProps
  } = props

  const { subscriptionsService } = services
  const subscriptions = subscriptionsService.selectors.getStoreSubscriptions()
  const { footer, isGiftingEnabled, isPurchasingEnabled } = config.subscriptions

  // Inject the default header value into the step header
  const i18nWithDefaultHeaderInSteps = SUBSCRIPTION_STEPS.reduce((newI18n, step) => {
    return {
      ...newI18n,
      [step.key]: {
        ...i18n[step.key],
        header: {
          default: {
            ...i18n.header,
          },
          ...(i18n[step.key] && i18n[step.key].header),
        },
      },
    }
  }, i18n)

  return (
    <div css={styles.root}>
      <div css={styles.container}>
        <Checkout
          steps={SUBSCRIPTION_STEPS}
          paths={Object.values(SUBSCRIPTION_PATHS)}
          i18n={i18nWithDefaultHeaderInSteps}
          subscriptions={subscriptions}
          services={services}
          config={config}
          isGiftable={isGiftingEnabled && isGiftable}
          isPurchasable={isPurchasingEnabled && isPurchasable}
          {...config.subscriptions}
          {...restProps}
        />
        <Footer css={styles.footer} i18n={i18n.footer} {...footer} {...restProps} />
      </div>
    </div>
  )
}

SubscriptionCheckout.defaultProps = {
  isGiftable: true,
  isPurchasable: true,
}

SubscriptionCheckout.propTypes = {
  /** Services for use with SubscriptionCheckout */
  services: PropTypes.shape({
    // eslint-disable-next-line react/forbid-prop-types
    loadingService: PropTypes.object,
    // eslint-disable-next-line react/forbid-prop-types
    subscriptionsService: PropTypes.object,
  }),
  /** Styles to override the default styles */
  // eslint-disable-next-line react/forbid-prop-types
  styles: PropTypes.object,
  /** Configs to pass to the components */
  // eslint-disable-next-line react/forbid-prop-types
  config: PropTypes.object,
  /** Language overrides */
  i18n: PropTypes.shape({
    // eslint-disable-next-line react/forbid-prop-types
    footer: PropTypes.object,
    // eslint-disable-next-line react/forbid-prop-types
    header: PropTypes.object,
  }),
  /** Whether subscriptions can be gifted */
  isGiftable: PropTypes.bool,
  /** Whether subscriptions can be purchased */
  isPurchasable: PropTypes.bool,
}

export default memo(SubscriptionCheckout)
