import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('FulfillmentAddressControllerModule', [])
  .controller(
    'FulfillmentAddressController',
    function (
      $q,
      $rootScope,
      $scope,
      $state,
      $stateParams,
      Restangular,
      addressList,
      addressValidationService,
      analytics,
      auth,
      cartService,
      checkoutAnalytics,
      compareCartService,
      config,
      errorModalService,
      lang,
      registrationService,
      reservationService,
      shoppingSelectorService,
      storesService
    ) {
      $scope.auth = auth
      $scope.cartService = cartService
      $scope.config = config
      $scope.reservationService = reservationService
      $scope.state = $state
      $scope.stateParams = $stateParams
      $scope.shoppingSelectorService = shoppingSelectorService
      $scope.shoppingIntent = auth.user.context.intent
      $scope.showMultipleDeliveryAddresses =
        $scope.shoppingIntent === 'delivery' && config.enableMultipleDeliveryAddresses
      $scope.showStoreMap =
        $scope.shoppingIntent === 'pickup' || config.checkout.showMapWhenDelivery
      $scope.addressValidationModalClosed = true
      $scope.selectedDeliveryAddress = {}
      // Form is added in a child scope.
      $scope.forms = {}

      // New users can occasionally hit the checkout with a missing context. If this
      // happens let's just hard set it to the defaultShoppingContext.
      // TODO: Try to find the source of this bug, it seems to be HEB specific &
      // only occurs about 10% of the time on new user registrations...
      if (!auth.user.context.intent) {
        shoppingSelectorService.forceContextIntentSet()
        $rootScope.reloadState()
      }

      // Construct regex object for validating postal codes

      $scope.postalCodePattern = new RegExp(config.postalCodePattern, 'i')

      // Copy attributes from the user object on init or from updated API response

      const initDeliveryAddressModel = sourceData => {
        $scope.deliveryAddress = {
          delivery_address: {
            first_name: sourceData.delivery_address.first_name,
            last_name: sourceData.delivery_address.last_name,
            company_name: sourceData.delivery_address.company_name,
            address1: sourceData.delivery_address.address1,
            address2: sourceData.delivery_address.address2,
            city: sourceData.delivery_address.city,
            province: sourceData.delivery_address.province,
            postal_code: sourceData.delivery_address.postal_code,
            instructions: sourceData.delivery_address.instructions,
          },
        }

        _.each(config.deliveryAddressPhoneNumberFields, field => {
          $scope.deliveryAddress[field.key] = sourceData[field.key]
        })

        if (sourceData.primary_number_key) {
          $scope.deliveryAddress.primary_number_key = sourceData.primary_number_key
        } else {
          $scope.deliveryAddress.primary_number_key = config.phone.defaultRequired
        }
      }

      initDeliveryAddressModel($scope.auth.user)

      if (!$scope.showMultipleDeliveryAddresses) {
        $scope.selectedDeliveryAddress = $scope.deliveryAddress.delivery_address
      }

      const getInitDeliveryAddress = () => {
        const contextAddress = auth.getAddressFromContext()

        return contextAddress.id ? addressList.getAddressFromId(contextAddress.id) : contextAddress
      }

      $scope.initialDeliveryAddress = getInitDeliveryAddress()

      $scope.deliveryAddressChanged = address => {
        $scope.selectedDeliveryAddress = address
      }

      // Validate and save delivery address / contact information

      const getDeliveryAddress = () =>
        $scope.showMultipleDeliveryAddresses
          ? $scope.selectedDeliveryAddress
          : $scope.deliveryAddress.delivery_address
      const goToNextStep = () => {
        checkoutAnalytics.editFulfillmentOptions({ success: true })
        return $state.go('main.checkout.reservation.confirmation')
      }

      $scope.saveDeliveryAddress = function () {
        const isAddressDifferentFromContext = (address, context) => {
          // Postal code has changed or
          // requireFullAddress and address1/street_address changed.
          return (
            address.postal_code !== context.deliveryPostalCode ||
            (config.shoppingContextProcess.delivery.requireFullAddress &&
              address.address1 !== context.street_address)
          )
        }

        // If delivery, confirm address/postal_code hasn't changed and context address is still valid.
        // If pickup, continue to confirmation.
        const proceed = function () {
          // Multiple delivery addresses is enabled && delivery
          if ($scope.showMultipleDeliveryAddresses) {
            const postalCodeChanged =
              $scope.selectedDeliveryAddress.postal_code !==
              $scope.auth.user.context.deliveryPostalCode
            const addressChanged =
              $scope.selectedDeliveryAddress.postal_code !==
                $scope.auth.user.context.deliveryPostalCode ||
              $scope.selectedDeliveryAddress.id !== $scope.auth.user.context.address_id ||
              $scope.selectedDeliveryAddress.address1 !== $scope.auth.user.context.street_address
            $scope.checkDelivery(addressChanged, postalCodeChanged)
          } else if ($scope.shoppingIntent === 'delivery') {
            const addressChanged = isAddressDifferentFromContext(
              $scope.deliveryAddress.delivery_address,
              $scope.auth.user.context
            )
            $scope.checkDelivery(addressChanged, addressChanged)
          } else {
            // Pickup mode
            goToNextStep()
          }
        }

        // Save user and continue.
        const updateUser = updateUserPayload => {
          auth.updateProfile(updateUserPayload).then(
            deliveryAddressResponse => {
              initDeliveryAddressModel(deliveryAddressResponse)
              proceed()
            },
            response => {
              if (response.error === 40304) {
                registrationService.promptLogin(
                  'main.checkout.reservation.address',
                  '',
                  '',
                  '',
                  lang.values.loginToContinue
                )
              } else {
                errorModalService.open(
                  response.data ? response.data.message : response.message,
                  'OK'
                )
              }
            }
          )
        }

        // Multiple delivery addresses enabled and in delivery mode.
        if ($scope.showMultipleDeliveryAddresses) {
          if (addressList.isDeliveryAddressComplete($scope.selectedDeliveryAddress)) {
            // Validate if validation is enabled, else proceed.
            if (
              config.enableExternalAddressValidation &&
              !addressList.checkAddressValidated($scope.selectedDeliveryAddress.id)
            ) {
              addressList
                .validateAddressAndSaveIfChanged($scope.selectedDeliveryAddress)
                .then(proceed)
            } else {
              proceed()
            }
          } else {
            errorModalService.open('Please fill out all required fields in the address', 'OK')
          }
        } else if (
          auth.validateFulfillmentContactInfo($scope.shoppingIntent, $scope.deliveryAddress)
        ) {
          const formIsDirty =
            $scope.forms.deliveryAddressForm && $scope.forms.deliveryAddressForm.$dirty
          // Validate address only if validation is enabled and delivery mode
          if (config.enableExternalAddressValidation && $scope.shoppingIntent === 'delivery') {
            const originalAddress = angular.copy($scope.deliveryAddress.delivery_address)
            addressList.validateAddress($scope.deliveryAddress.delivery_address).then(address => {
              if (
                addressValidationService.areAddressesSame(originalAddress, address) &&
                !formIsDirty
              ) {
                proceed()
              } else {
                updateUser($scope.deliveryAddress)
              }
            })
            // If address validation is disabled and the form is $dirty, updateUser
          } else if (formIsDirty) {
            updateUser($scope.deliveryAddress)
          } else {
            proceed()
          }
        } else {
          errorModalService.open('Please fill out all required fields', 'OK')
        }
      }

      // Check if a preferred phone number has been set, if it hasn't, use the number type of the input box that is calling this function.
      // Only do this if they haven't already selected their preferred phone number

      $scope.setPreferredPhoneNumber = function (numberType) {
        if ($scope.deliveryAddress.primary_number_key === null) {
          $scope.deliveryAddress.primary_number_key = numberType
        }
      }

      const showAddressChangedModal = address => {
        const { address1, city, province, postal_code } = address
        const { deliveryMismatchedPostalCodes, yes, no } = lang.values
        const message = `${deliveryMismatchedPostalCodes}<br /><br />${address1}<br />${city}, ${province} ${postal_code}`
        return errorModalService.open(message, yes, no)
      }

      const showStoreChangedModal = store => {
        const { storeSuggestionError, yes, no } = lang.values
        const message = `${storeSuggestionError}<br /><br />${store.name}`
        return errorModalService.open(message, yes, no)
      }

      // Check if there is an available store to deliver to this postal code
      $scope.checkDelivery = (addressUpdated, postalCodeChanged) => {
        const deliveryAddress = getDeliveryAddress()
        const addressParams = auth.getContextFromAddress(deliveryAddress)
        const addressUrlParams = auth.getUrlParamsFromAddress(deliveryAddress)
        // Get stores.
        return storesService
          .fetch({
            delivery_postal_code: addressUrlParams.delivery_postal_code,
            show_delivery: true,
          })
          .then(
            stores => {
              // Check if there is a store that can deliver to that postal code
              if (stores.length === 0) {
                return errorModalService.open(lang.values.noDeliveryToPostalCodeError, 'OK')
              }
              // There are stores, check if user store is in the list
              const storeUnchanged = !!_.find(stores, { id: auth.user.store.id })

              // Address AND store are the same.
              if (storeUnchanged && !addressUpdated) {
                return goToNextStep()
              }
              if (storeUnchanged) {
                const cartCompareCheck = () =>
                  compareCartService
                    .compareCart(true, 'address', addressUrlParams)
                    .then(() => auth.setContext(addressParams))
                    .then(goToNextStep)
                if (postalCodeChanged) {
                  return showAddressChangedModal(deliveryAddress).then(cartCompareCheck)
                }
                return cartCompareCheck()
              }
              return (
                showAddressChangedModal(deliveryAddress)
                  .then(() => showStoreChangedModal(stores[0]))
                  // cartCompare store should be enough if the address and store have changed.
                  .then(() =>
                    compareCartService.compareCart(true, 'store', {
                      ...addressUrlParams,
                      store_id: stores[0].id,
                    })
                  )
                  // Make these two requests at the same time possibly causes a race condition.
                  .then(() => auth.setContext(addressParams))
                  .then(() => auth.update({ store_id: stores[0].id }))
                  .then(goToNextStep)
              )
            },
            response => {
              // Checking available stores for postal code failed.
              errorModalService.open(response.data ? response.data.message : response.message, 'OK')
              checkoutAnalytics.editFulfillmentOptions({ success: false })
            }
          )
      }
    }
  )
