import angular from 'angular'
import _ from 'lodash'

export default angular
  .module('PaymentGatewayServiceModule', [])
  .provider('paymentGateway', function paymentGateway() {
    const provider = this
    const gateways = []

    /**
     * Registers a gateway based on the options provided
     * @param {Object} gateway
     *         name: '<name>',                              // General name of the payment method
     *         key: '<payment type as returned by server>', // Key as returned by server, e.g. auth_capture
     *         serviceName: '<service name>',               // The service name, angularjs injectable
     *         newCheckoutCssClass: '<string>',             // Simplified checkout parent class (default: provider-generic_styling)
     *         hideSaveButton: <true|false>,                // Don't show the payment form save button (default: false)
     *         hideGiftCardLink: <true|false>,              // Don't show the gift card link under this payment method (default: false)
     *         noSettingsRequired: <true|false>,            // Indicates this payment can be auto saved, without allowing to edit (default: false)
     */
    provider.registerGateway = function (gateway) {
      gateways.push(gateway)
    }

    provider.$get = [
      '$injector',
      function ($injector) {
        const service = this

        service.getByName = function (name) {
          return gateways.find(gateway => gateway.name === name)
        }

        service.get = function (key) {
          return _.find(gateways, { key })
        }

        service.all = function () {
          return angular.copy(gateways)
        }

        service.getTemplate = function (key) {
          const gateway = service.get(key)

          if (gateway) {
            return gateway.template || ''
          }
          gateWayNotFound(key)
        }

        service.getService = function (key) {
          const gateway = service.get(key)
          if (gateway) {
            return $injector.get(gateway.serviceName)
          }
          gateWayNotFound(key)
        }

        return service
      },
    ]
  })

function gateWayNotFound(key) {
  throw new ReferenceError(`Could not find payment gateway, as specified by the key "${key}"`)
}
