// Helper for keyboard a11y such that focus returns to invoking element
//

import angular from 'angular'
import { CONTENT_ELEMENT } from '@app/angular/constants/elementSelectors'

export default angular
  .module('refocusComponentModule', [])
  .directive('refocus', (mobileNavService, refocusService) => ({
    link: (scope, el) => {
      scope.refocusService = refocusService

      el.on('click', event => {
        // "Save" last clicked element with the `refocus` directive attached
        refocusService.refocusElement = event.target
      })

      scope.$watch('refocusService.triggerRefocus', newVal => {
        const isSlideOutMenuItem = document
          .querySelector('#mobile-side-nav')
          ?.contains(refocusService.refocusElement)

        if (newVal === true) {
          // Open slide-out menu if it is currently closed
          if (isSlideOutMenuItem && mobileNavService.slideOutMenuIsVisible === false) {
            mobileNavService.showSlideOutMenu()
          }

          // refocus on last clicked element then reset triggers and refocus element
          refocusService.refocusElement.focus()
          refocusService.triggerRefocus = false
          refocusService.refocusElement = document.querySelector(CONTENT_ELEMENT)
        }
      })
    },
  }))
  .service('refocusService', function refocusService() {
    const service = this

    service.refocusElement = document.querySelector(CONTENT_ELEMENT)
    service.triggerRefocus = false

    // Method used to initialize `modal.closing` listener in controller scope
    service.setRefocusTrigger = scope =>
      scope.$on('modal.closing', () => {
        service.triggerRefocus = true
      })
  })
