import angular from 'angular'
import { UNIT_OF_MEASURE } from '@app/angular/constants/uom'
import SharePlugin from '@app/react/mobile/plugins/sharePlugin'
import isCordova from '@app/utils/isCordova'
import isCapacitor from '@app/utils/isCapacitor'
import template from './shareListButton.html'

export default angular
  .module('ShareListButtonModule', [])
  .directive('shareListButton', function () {
    return {
      scope: {
        customItems: '&',
        listTitle: '<',
        sortedProducts: '<',
        sortedCategories: '<',
        hideText: '<',
      },
      replace: true,
      template,
      controller($scope, cartService, $filter, lang, $window) {
        $scope.lang = lang

        // FIXME - Clean this up so it can be more re-usable.

        $scope.shareListToMobile = function () {
          // Use Title for title of page and subject line of email

          const title = $scope.listTitle || lang.values.shareListTitle

          // Build content string with "custom" items and regular items broken down by category

          let content = `${title}\n\n`

          // Loop through any customer items and add them with the heading.

          if ($scope.customItems()?.length > 0) {
            content += lang.values.customItem.toUpperCase()
            content += '\n\n'
            $scope.customItems().forEach(item => {
              content += '\u2022 '
              content += item.pseudo_product.name
              content += '\n'
            })
            content += '\n\n'
          }

          // Loop through the categorized products and make heading for each then loop through and display products in category.
          if ($scope.sortedCategories.length > 0) {
            $scope.sortedCategories.forEach(category => {
              content += category.toUpperCase()
              content += '\n\n'
              $scope.sortedProducts[category].forEach(product => {
                content += '\u2022 '
                content += product.store_product.name
                content += ' - '
                content += getFormattedPrice(product)
                content += ' '
                content += getFormattedQuantity(product)
                content += '\n'
              })
              content += '\n\n'
            })
            content += lang.values.estimatedTotal
            content += '\n'
            content += $filter('currency')(cartService.cart.product_total)
          }

          // Use cordova social sharing plugin, passing in "content" and "subject" parameters
          if (isCordova()) {
            $window.plugins.socialsharing.share(content, title)
          } else if (isCapacitor()) {
            SharePlugin.share({ title, content })
          }
        }

        // Format the price depending upon whether it is on sale or not and whether is has base quantity of one or more than 1

        function getFormattedPrice(product) {
          let formattedPrice
          if (product.store_product.sale_price) {
            // eslint-disable-next-line eqeqeq
            if (product.store_product.sale_quantity == 1) {
              formattedPrice = `${$filter('currency')(product.store_product.sale_price)}/${
                product.store_product.display_uom
              }`
            } else if (product.store_product.sale_quantity > 1) {
              formattedPrice = `${product.store_product.sale_quantity} `
              formattedPrice +=
                // eslint-disable-next-line eqeqeq
                product.store_product.display_uom != UNIT_OF_MEASURE.EACH
                  ? product.store_product.display_uom
                  : ''
              formattedPrice += ` for ${$filter('currency')(product.store_product.sale_price)}`
            }
            // eslint-disable-next-line eqeqeq
          } else if (product.store_product.base_quantity == 1) {
            formattedPrice = `${$filter('currency')(product.store_product.base_price)}/${
              product.store_product.display_uom
            }`
          } else if (product.store_product.base_quantity > 1) {
            formattedPrice = `${product.store_product.base_quantity} `
            formattedPrice +=
              // eslint-disable-next-line eqeqeq
              product.store_product.display_uom != UNIT_OF_MEASURE.EACH
                ? product.store_product.display_uom
                : ''
            formattedPrice += ` for ${$filter('currency')(product.store_product.base_price)}`
          }
          return formattedPrice
        }

        // Display a quantity unit if the display unit of measure is not each

        function getFormattedQuantity(product) {
          let formattedQty = `x ${product.quantity} `
          formattedQty +=
            // eslint-disable-next-line eqeqeq
            product.store_product.display_uom.toLowerCase() != UNIT_OF_MEASURE.EACH
              ? `${product.store_product.display_uom} `
              : ''
          return formattedQty
        }
      },
    }
  })
