import angular from 'angular'

export default angular.module('OverlayModule', []).directive('overlay', function ($document) {
  return {
    restrict: 'A',
    scope: true,
    link(scope, element, _) {
      function onClick() {
        const sideCartContainer = $document[0].getElementsByClassName('slideInRight')[0]
        sideCartContainer.classList.remove('hover')
        scope.$root.isSideCartOpen = false
        scope.$root.$apply()
      }

      element.on('click', onClick)
    },
  }
})
