import angular from 'angular'
import template from '../../components/shared/facetedFilterModal/facetedFilterModal.html'

export default angular
  .module('facetedFilterModalServicesModule', [])
  .service(
    'facetedFilterModalService',
    function facetedFilterModalService($uibModal, uibModalAriaLabelService) {
      /**
       * Opens a faceted filter modal dialog.
       * @param {FilterOptions} options Options to pass to modal
       * @return {Promise<FilterResults>} The new filter options to be used
       */
      this.open = function (options) {
        return $uibModal.open({
          resolve: {
            filterModalOptions() {
              return options
            },
            ariaLabel: () => {
              uibModalAriaLabelService.setLabel('Filters')
            },
          },
          controller: 'facetedFilterModalController',
          template,
        }).result
      }

      return this
    }
  )
