import angular from 'angular'

export default angular
  .module('FacetedFilterModalControllerModule', [])
  .controller('facetedFilterModalController', function ($scope, $timeout, filterModalOptions) {
    const filterModalElem = () => document.querySelector('button.close')
    $scope.filterModalOptions = filterModalOptions

    // Focus on modal close button after the template has been loaded
    $timeout(() => {
      filterModalElem().focus()
    })
  })
