import angular from 'angular'
import { SHOPPING_MODES, CART_COMPARE_CHANGES } from '@app/angular/constants/context'
import { SHOPPING_SELECTOR_STAGES } from '@app/angular/constants/shoppingSelectorStages'

export default angular
  .module('StoreSelectorControllerComponentModule', [])

  .controller(
    'StoreSelectorController',
    function (
      $q,
      $scope,
      $state,
      analytics,
      auth,
      cartListTransferService,
      cartService,
      compareCartService,
      config,
      eComLightService,
      lang,
      manualUpsellService,
      modalOptions,
      placementsService,
      shoppingContextService,
      storesService
    ) {
      // Check whether the shopping context is instore, delivery or pickup. For delivery, we are just showing the zip code, and then
      // upon submitting it gets selected and we proceed to the timeslot
      // For pickup and instore, we show the zip, but then when you submit you reveal the store list and they select it.
      // After selecting, we move along.

      $scope.lang = lang
      let setAddress
      if (
        auth.getContext().intent === undefined &&
        !shoppingContextService.selectedFulfillmentType
      ) {
        $scope.$parent.setShoppingSelectorStage(SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT)
      } else {
        $scope.shopContextIntent =
          shoppingContextService.selectedFulfillmentType || auth.getContext().intent
      }

      if ($scope.shoppingSelectorStage === SHOPPING_SELECTOR_STAGES.BANNER_SELECTOR) {
        // Came back from timeslot picker.
        const address = auth.getAddressFromContext(auth.user.context)
        storesService
          .fetchBanners({
            ...auth.getUrlParamsFromAddress(address),
            show_delivery: true,
          })
          .then(storeBanners => {
            $scope.setDeliveryBannersAndAddress(storeBanners.items, address)
          })
        $scope.$parent.clearShoppingSelectorStageErrorMessage(
          SHOPPING_SELECTOR_STAGES.BANNER_SELECTOR
        )
      }

      $scope.onShopAnotherWay = () => {
        $scope.$parent.setShoppingSelectorStage(SHOPPING_SELECTOR_STAGES.SHOPPING_CONTEXT)
      }

      $scope.setDeliveryBannersAndAddress = (banners, address) => {
        setAddress = address
        $scope.availableBannerSlugs = banners
        $scope.availableBannersCopy = lang.template(lang.values.availableBannerCountMessage, {
          bannerCount: $scope.availableBannerSlugs.length,
        })
      }

      $scope.bannerSelectedFromBannerList = bannerSlug => {
        storesService
          .fetch({
            delivery_postal_code: setAddress.postal_code,
            retailer_slug: bannerSlug,
            show_delivery: true,
          })
          .then(stores => {
            if (stores.length > 0) {
              $scope.updateDeliveryStore(stores[0], setAddress)
            } else {
              // Partial Zip and delivery is not available.
              $scope.setShoppingSelectorErrorMessage(
                SHOPPING_SELECTOR_STAGES.BANNER_SELECTOR,
                lang.values.deliveryUnavailableAtAddress
              )
            }
          })
      }

      $scope.updateDeliveryStore = (store, address) => {
        const params = auth.getUrlParamsFromAddress(address)
        const contextParams = auth.getContextFromAddress(address)
        const fulfillmentType =
          shoppingContextService.selectedFulfillmentType || auth.getContext().intent

        params.store_id = store.id
        params.delivery_address = address
        params.fulfillment_type =
          fulfillmentType === SHOPPING_MODES.CATERING ? SHOPPING_MODES.PICKUP : fulfillmentType

        contextParams.intent = fulfillmentType

        if (store.store_banner && store.store_banner.key) {
          contextParams.retailer_slug = store.store_banner && store.store_banner.key
        }

        return compareCartService
          .compareCart(true, CART_COMPARE_CHANGES.STORE, params)
          .then(transferData => {
            // shortcut to closed on a cancel
            if (transferData.dismissed) throw new Error('modal dismissed')

            return transferData
          })
          .then(transferData => _handleCartCompareConfirmation(transferData))
          .then(() => changeHomeStore(store, fulfillmentType, contextParams))
          .catch($scope.$close)
      }

      const handleCustomFulfillmentTypeFlow = () => {
        const shoppingModeConfig =
          config.shoppingSelector[shoppingContextService.selectedFulfillmentType]

        eComLightService.setMode(
          shoppingContextService.selectedFulfillmentType === SHOPPING_MODES.CATERING
        )

        if (shoppingModeConfig.redirectTo) {
          $state.go(shoppingModeConfig.redirectTo, shoppingModeConfig.redirectParams)
        }
      }

      $scope.updateHomeStore = store => {
        const { deliveryPostalCode } = auth.user.context
        const fulfillmentType =
          shoppingContextService.selectedFulfillmentType ||
          shoppingContextService.getIntentFromContext()

        return compareCartService
          .compareCart(true, CART_COMPARE_CHANGES.STORE, {
            store_id: store.id,
            fulfillment_type: fulfillmentType,
            delivery_postal_code: deliveryPostalCode,
          })
          .then(transferData => {
            // shortcut to closed on a cancel
            if (transferData.dismissed) throw new Error('modal dismissed')

            return transferData
          })
          .then(transferData => _handleCartCompareConfirmation(transferData))
          .then(() => changeHomeStore(store, fulfillmentType))
          .then(() => handleCustomFulfillmentTypeFlow())
          .catch($scope.$close)
      }

      const _handleCartCompareConfirmation = transferData => {
        const { shopping_mode: initialFulfillmentType } = auth.getContext()
        const { selectedFulfillmentType } = shoppingContextService
        const fulfillmentTypesShowTransferModal = [
          SHOPPING_MODES.INSTORE_LIST,
          SHOPPING_MODES.CATERING,
        ]
        const showCartListTransfer =
          // We should only show the transfer modal if we are switching in/out of catering or instore list
          [initialFulfillmentType, selectedFulfillmentType].some(type =>
            fulfillmentTypesShowTransferModal.includes(type)
          ) &&
          initialFulfillmentType !== selectedFulfillmentType &&
          (transferData.numTotal > 0 || cartService.countItems() > 0)

        if (!showCartListTransfer) {
          return $q.resolve()
        }

        $scope.$parent.setShoppingSelectorStage(SHOPPING_SELECTOR_STAGES.CART_TO_LIST_MIGRATION)

        return cartListTransferService.initializeTransfer({
          initialContext: initialFulfillmentType,
          updatedContext: selectedFulfillmentType,
          transferData,
        })
      }

      // Triage whether to proceed to timeslot depending on the shopContextIntent and the shoppingContextProcess config

      function proceedToTimeslot() {
        const isTimeslotSelectionDisabled =
          config.shoppingContextProcess[$scope.shopContextIntent]?.hideTimeslot

        modalOptions.reset()

        if (
          (auth.user.status === 'trial' && config.hideTimeslotReservationFromTrialUsers) ||
          $scope.shopContextIntent === SHOPPING_MODES.INSTORE_LIST ||
          isTimeslotSelectionDisabled
        ) {
          $scope.$dismiss()
        } else {
          $scope.$parent.setShoppingSelectorStage(SHOPPING_SELECTOR_STAGES.TIMESLOT)
        }
      }

      // analytics for store selection
      const selectStoreAnalytics = storeid => {
        analytics.logSegmentEvent('availability.select_store', { storeid })
      }

      // Change the home store via auth service
      function changeHomeStore(store, fulfillmentType = null, context = {}) {
        const { selectedFulfillmentType } = shoppingContextService

        if (!auth.user) {
          // We're creating a trial user, just return the store
          $scope.$close(store)
        } else {
          const oldStore = auth.user.store

          selectStoreAnalytics(store.id)

          // Update the store in the backend
          return (
            auth
              .update({ store_id: store.id, has_changed_store: true })
              .then(() => {
                const newContext = {
                  ...auth.getContext(),
                  ...context,
                  storeNumber: store.id,
                }

                if (selectedFulfillmentType !== SHOPPING_MODES.DELIVERY) {
                  newContext.deliveryPostalCode = ''
                }

                if (fulfillmentType) {
                  if (fulfillmentType === SHOPPING_MODES.CATERING) {
                    newContext.shopping_mode = SHOPPING_MODES.CATERING
                  } else {
                    newContext.shopping_mode = selectedFulfillmentType
                  }
                  newContext.intent = fulfillmentType
                }
                const updatedContext = auth.setContext(newContext)
                auth.sendUserUpdatedStore(auth.user)
                return updatedContext
              })
              .then(auth.sendAvailableStoreContexts(auth.user))
              .then(cartService.fetch)
              .then(proceedToTimeslot)
              // Update the upsell items if the store has changed
              .then(
                () =>
                  config.enableCheckoutManualUpsell &&
                  manualUpsellService.getUpsellProducts(oldStore.id !== store.id)
              )
              // Update the featured departments if the store has changed
              .then(placementsService.getFeaturedDepartments(oldStore.id !== store.id))
          )
        }
      }
    }
  )
  .run($templateCache => {
    $templateCache.put('storeSelectorMap.html', require('./storeSelectorMap.html'))
    $templateCache.put('storeSelectorList.html', require('./storeSelectorList.html'))

    $templateCache.put(
      'storeSelectorMapStores.mobile.html',
      require('./storeSelectorMapStores.mobile.html')
    )
    $templateCache.put(
      'storeSelectorMapStores.desktop.html',
      require('./storeSelectorMapStores.desktop.html')
    )

    $templateCache.put(
      'storeSelectorListStores.desktop.html',
      require('./storeSelectorListStores.desktop.html')
    )
    $templateCache.put(
      'storeSelectorListStores.mobile.html',
      require('./storeSelectorListStores.mobile.html')
    )
  })
