import angular from 'angular'
import {
  EXPRESS_SUBSCRIPTION_STEPS,
  EXPRESS_SUBSCRIPTION_STEP_ORDER,
  MEMBERSHIP_STATUSES,
  EXPIRATION_DATE_FORMAT,
  EXPRESS_SUBSCRIPTION_PLAN_TERMS,
} from '@app/angular/constants/express'
import { SSO_PROVIDERS } from '@app/angular/constants/ssoProviders'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { getConfigByRule } from '@app/services/express/utils'
import template from './icExpressEntry.html'
import expressSignupModal from '../views/expressSignupModal.html'
import expressGiftCardRedeemModalTemplate from '../views/expressGiftCardRedeemModal.html'

class IcExpressEntry {
  constructor(
    $filter,
    $sce,
    $scope,
    $state,
    $uibModal,
    config,
    auth,
    express,
    registrationService,
    socialSignOnService,
    lang,
    mobileService,
    utilService
  ) {
    Object.assign(this, {
      $sce,
      $state,
      $uibModal,
      config,
      auth,
      express,
      registrationService,
      socialSignOnService,
      lang,
      mobileService,
      currencyFilter: $filter('currency'),
      userCurrentSubscription: express.userCurrentSubscription,
      userSubscriptionStatus: null,
      adList: [],
      currentStep: EXPRESS_SUBSCRIPTION_STEPS.AD,
      useIcTheme: !config.icExpress.enableRetailerTheme,
      EXPRESS_SUBSCRIPTION_STEPS,
      isLoading: true,
      EXPIRATION_DATE_FORMAT,
      eligibleSubscriptionPlans: undefined,
      planIdToSubscribeTo: undefined,
      utilService,
    })
  }

  $onInit() {
    if (EXPRESS_SUBSCRIPTION_STEP_ORDER.includes(this.initialStep)) {
      this.currentStep = this.initialStep
    }
    // If the user is not logged in or we are only allowing gift card registration, populate with default values. Otherwise get their subscription information.
    if (!this.auth.user.ic_linked) {
      this.userSubscriptionStatus = MEMBERSHIP_STATUSES.UNAUTHENTICATED
      this.userCurrentSubscription = null
      this.adList = this.config.icExpress.ad.valuePropositions.unauthenticated
      this.isLoading = false
    } else if (this.config.icExpress.giftCardOnlyRegistration) {
      this.userSubscriptionStatus = null
      this.userCurrentSubscription = null
      this.adList = this.config.icExpress.ad.valuePropositions.giftCardOnlyRegistration
      this.isLoading = false
    } else {
      Promise.all([
        this.express.getCurrentSubscription(true),
        this.express.fetchEligibleSubscriptionPlans(),
      ])
        .then(results => {
          this.userSubscriptionStatus = this.express.getMembershipStatus()
          this.userCurrentSubscription = this.express.userCurrentSubscription
          this.eligibleSubscriptionPlans = results[1]

          // Filter out trial plans if we're not trial eligible. We can occasionally get a promo trial plan returned in the response even if we're not trial eligible
          if (!this.express.isTrialEligible()) {
            this.eligibleSubscriptionPlans = this.eligibleSubscriptionPlans.filter(
              plan => plan.polytype !== EXPRESS_SUBSCRIPTION_PLAN_TERMS.TRIAL
            )
          }

          // Set the default subscription plan
          this.planIdToSubscribeTo = this.eligibleSubscriptionPlans[0].id
          if (this.express.isTrialEligible()) {
            // If we're trial eligible, the default plan is always trial
            this.express.getPlanIds(EXPRESS_SUBSCRIPTION_PLAN_TERMS.TRIAL).then(planId => {
              if (planId) this.planIdToSubscribeTo = planId
            })
          } else if (this.config.icExpress.defaultTermLengthToPurchase) {
            // Otherwise, we try and look up the id based on the value in the config
            this.express
              .getPlanIds(this.config.icExpress.defaultTermLengthToPurchase)
              .then(planId => {
                if (planId) this.planIdToSubscribeTo = planId
              })
          }

          this.express.getAdList().then(adList => {
            this.adList = adList
          })
        })
        .finally(() => (this.isLoading = false))
    }

    // build Instacart Link Button tooltip description
    const tooltipDescription = this.lang.templateAsString(
      this.config.icExpress.ad.icLinkButtonDescription,
      {
        retailer: this.lang.values.clientName,
      }
    )
    const tooltipTitle = this.mobileService.isMobile ? '' : this.ctaLinkButtonLabel

    this.ctaLinkButtonDescription = this.$sce.trustAsHtml(`
      <div class="ic-express-link-account-description">
        <div class="ic-express-link-account-description__title">${tooltipTitle}</div>
        <div>${tooltipDescription}</div>
      </div>
    `)
  }

  /**
   * Handles IC Express subscription success by updating
   * the user current subscription and showing a success page
   *
   * @return { undefined }
   */
  handleSubscriptionSuccess() {
    this.express.getCurrentSubscription(true)
    this.nextStep()
  }

  /**
   * Takes the user to the previous Express step
   *
   * @returns { undefined }
   */
  previousStep() {
    this.currentStep = this._getPreviousStep(this.currentStep)
  }

  /**
   * Takes the user to the next Express step
   *
   * @returns { undefined }
   */
  nextStep() {
    this.currentStep = this._getNextStep(this.currentStep)
  }

  /**
   *
   * @param { string } currentStep
   * @returns { string } A string that represents the previous step
   */
  _getPreviousStep(currentStep) {
    const { AD, FORM, SUCCESS } = EXPRESS_SUBSCRIPTION_STEPS

    switch (currentStep) {
      case SUCCESS:
        return FORM
      case FORM:
        return AD
      default:
        console.error(`There's no previous step for the ${currentStep} step`)
        return ''
    }
  }

  /**
   *
   * @param { string } currentStep
   * @returns { string } A string that represents the next step
   */
  _getNextStep(currentStep) {
    const { AD, FORM, SUCCESS } = EXPRESS_SUBSCRIPTION_STEPS

    switch (currentStep) {
      case AD:
        return FORM
      case FORM:
        return SUCCESS
      default:
        console.error(`There's no next step for the ${currentStep} step`)
        return ''
    }
  }

  // This needs to be an arrow function in order to keep a correct reference to this when used in sub components
  planSelected = planId => {
    this.planIdToSubscribeTo = planId
  }

  /**
   * Called when the cta button is clicekd. If we are not logged
   * it will display the login dialog. Otherwise, it will go to the
   * next step
   *
   * @returns { undefined }
   */
  ctaButtonClicked() {
    if (this.userSubscriptionStatus === MEMBERSHIP_STATUSES.UNAUTHENTICATED) {
      this.registrationService
        .open('login', null, null, null, null, null, LOGIN_CONTEXTS.INSTACART_EXPRESS)
        .then(this.$state.reload)
    } else if (this.config.icExpress.giftCardOnlyRegistration) {
      this.showGiftCardRedeemModal()
    } else if (this.config.icExpress.enableModalSignupFlow) {
      this.openSignupModal()
    } else {
      this.nextStep()
    }
  }

  ctaLinkButtonClicked() {
    this.socialSignOnService.handleSocialSignOn(SSO_PROVIDERS.INSTACART_LINK)
  }

  showGiftCardRedeemButton() {
    return (
      this.userSubscriptionStatus !== MEMBERSHIP_STATUSES.UNAUTHENTICATED &&
      this.config.icExpress.showExpressRedeemByGiftcard
    )
  }

  showAccountLinkButton() {
    return (
      this.userSubscriptionStatus !== MEMBERSHIP_STATUSES.UNAUTHENTICATED &&
      this.config.icExpress.showExpressAccountLink &&
      this.ctaLinkButtonLabel != null
    )
  }

  showGiftCardRedeemModal = () => {
    const parentThis = this
    return this.$uibModal.open({
      template: expressGiftCardRedeemModalTemplate,
      controller: [
        '$uibModalInstance',
        function ($uibModalInstance) {
          this.onCancel = () => {
            $uibModalInstance.close()
          }

          this.onSuccess = () => {
            $uibModalInstance.close()
            parentThis.$state.go('main.home', null, {
              reload: parentThis.$state.current.name === 'main.home',
            })
          }
        },
      ],
      controllerAs: '$ctrl',
      keyboard: false,
      backdrop: 'static',
    }).result
  }

  get title() {
    const {
      title,
      trialTitle,
      unauthenticatedTitle,
      giftCardOnlyRegistrationTitle,
    } = this.config.icExpress.ad

    let titleString = title
    if (this.currentStep === EXPRESS_SUBSCRIPTION_STEPS.SUCCESS) {
      titleString = this.config.icExpress.ad.successTitle
    } else if (this.userSubscriptionStatus === MEMBERSHIP_STATUSES.UNAUTHENTICATED) {
      titleString = unauthenticatedTitle
    } else if (this.config.icExpress.giftCardOnlyRegistration) {
      titleString = giftCardOnlyRegistrationTitle
    } else if (this.userSubscriptionStatus === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE)
      titleString = trialTitle

    return this.lang.templateAsString(titleString, {
      planPeriod: this.getPlanPeriod(),
    })
  }

  get subTitle() {
    const {
      unauthenticatedSubTitle,
      trialSubTitle,
      membershipSubTitle,
      giftCardOnlyRegistrationSubTitle,
    } = this.config.icExpress.ad

    let subTitle = membershipSubTitle
    if (this.userSubscriptionStatus === MEMBERSHIP_STATUSES.UNAUTHENTICATED) {
      subTitle = unauthenticatedSubTitle
    } else if (this.config.icExpress.giftCardOnlyRegistration) {
      subTitle = giftCardOnlyRegistrationSubTitle
    } else if (this.userSubscriptionStatus === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE)
      subTitle = trialSubTitle

    return this.lang.templateAsString(subTitle, { name: this.config.icExpress.name })
  }

  get ctaButtonLabel() {
    const { trialDays, name, ad } = this.config.icExpress
    const {
      unauthenticatedButtonLabel,
      trialButtonLabel,
      membershipButtonLabel,
      giftCardOnlyRegistrationButtonLabel,
      ctaButtonLabelVariants,
    } = ad

    let label = getConfigByRule(this.express.expressUserConfigContext, ctaButtonLabelVariants)

    if (!label) {
      label = membershipButtonLabel
      if (this.userSubscriptionStatus === MEMBERSHIP_STATUSES.UNAUTHENTICATED) {
        label = unauthenticatedButtonLabel
      } else if (this.config.icExpress.giftCardOnlyRegistration) {
        label = giftCardOnlyRegistrationButtonLabel
      } else if (this.userSubscriptionStatus === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE)
        label = trialButtonLabel
    }

    return this.lang.templateAsString(label, {
      days: trialDays,
      name,
    })
  }

  get ctaLinkButtonLabel() {
    const { ad } = this.config.icExpress
    const { icLinkButtonLabel, icLinkButtonLabelVariants } = ad
    // this.auth.user.ic_linked: True when user registers an account and user is created on carrot side
    // this.auth.user.ic_imported: True when user actually does the linking with own Instacart account

    if (!this.auth.user.ic_linked || this.auth.user.ic_imported) {
      return null
    }

    const labelVariant = getConfigByRule(
      this.express.expressUserConfigContext,
      icLinkButtonLabelVariants
    )

    return labelVariant || icLinkButtonLabel
  }

  get ctaLinkButtonShow() {
    return this.config.icExpress.ad.icLinkButtonEnabled && this.ctaLinkButtonLabel != null
  }

  get headerImage() {
    const image = getConfigByRule(
      { ...this.express.expressUserConfigContext, isMobile: this.mobileService.isMobile },
      this.config.icExpress.ad.headerImageVariants
    )
    return image ? this.utilService.loadAssetImage(image) : undefined
  }

  getDisclaimerText() {
    const { name, minimumOrderAmount, termsAndConditions } = this.config.icExpress
    const { trialText, membershipText } = termsAndConditions

    return this.lang.templateAsString(this.express.isTrialEligible() ? trialText : membershipText, {
      express: name,
      minOrder: this.currencyFilter(minimumOrderAmount, this.config.currencySymbol, 0),
      link: termsAndConditions.url,
      planPeriod: this.getPlanPeriod(),
    })
  }

  getPlanPeriod() {
    const { isTrialEligible, isMembershipEligible } = this.express
    const { price, recurrenceType } = this.config.icExpress.plans.yearly

    if (isTrialEligible() || isMembershipEligible()) {
      const amount = this.currencyFilter(price, this.config.currencySymbol, 0)

      return `${amount}/${recurrenceType}`
    }
  }

  /**
   * Called when the button on the final step is clicked
   *
   * @returns { undefined }
   */
  onClose() {
    const { $state } = this

    if (this.isGoToMainOnSuccess) {
      $state.go('main.home')
    } else {
      this.onSuccessRedirect()
    }
  }

  openSignupModal = () => {
    const planId = this.planIdToSubscribeTo
    const state = this.$state
    this.$uibModal.open({
      template: expressSignupModal,
      controller() {
        this.planId = planId
        this.onSuccess = () => {
          state.go('main.home', {}, { reload: true })
        }
        this.onCancel = () => {
          state.go('main.home', {}, { reload: true })
        }
      },
      controllerAs: '$ctrl',
      keyboard: false,
      backdrop: 'static',
      resolve: {},
    })
  }
}

export default angular.module('IcExpressEntryComponentModule', []).component('icExpressEntry', {
  bindings: {
    initialStep: '<?',
    onSuccessRedirect: '&?',
    isGoToMainOnSuccess: '<?',
  },
  template,
  controller: IcExpressEntry,
})
