import angular from 'angular'
import { loyalty } from '@instacart/enterprise-services'

import { USER_ACTIONS } from '../../constants/events'

export default angular
  .module('userServicesModule', [])
  .service('userService', function ($rootScope, config, localStorageService, ngPromise) {
    const service = this

    const PRIMARY_CARD_KEY = 'primary-card'

    service.loyaltyCards = []
    service.loyaltyPasses = []

    $rootScope.$on('auth', (event, action) => {
      if (action === USER_ACTIONS.USER_LOGGED_IN) {
        service.fetchLoyaltyCards()
        service.fetchLoyaltyPasses()
      }
    })

    $rootScope.$on('registered', () => {
      service.fetchLoyaltyCards()
      service.fetchLoyaltyPasses()
    })

    /**
     * Retrieves the user's loyalty card from localStorage if available.
     * Otherwise, tries to fetch the card from the user object.
     */
    service.getLoyaltyCard = function () {
      const card = localStorageService.deprecatedGet(PRIMARY_CARD_KEY)
      if (card) {
        return card
      }

      const primaryCard = service.getPrimaryLoyaltyCard()
      return primaryCard?.card_number ? primaryCard : ''
    }

    const getCards = ngPromise.wrapper(loyalty.api.loyaltyCards.getAll)
    service.fetchLoyaltyCards = () => {
      service.loyaltyCardsPromise = getCards().then(loyaltyCards => {
        service.loyaltyCards = loyaltyCards.loyalty_cards || []

        const loyaltyPointsMultiplier = config.loyaltyPointsMultiplier || 1
        service.loyaltyCards.forEach(loyaltyCard => {
          loyaltyCard.points_balance *= loyaltyPointsMultiplier
        })

        // Fetch and store the loyalty card
        const primaryCard = service.getPrimaryLoyaltyCard()
        if (primaryCard) {
          localStorageService.deprecatedSet(PRIMARY_CARD_KEY, primaryCard)
        } else {
          localStorageService.deprecatedRemove(PRIMARY_CARD_KEY)
        }
        return loyaltyCards
      })

      return service.loyaltyCardsPromise
    }

    service.getLoyaltyCards = () => service.loyaltyCardsPromise || service.fetchLoyaltyCards()

    const updateCards = ngPromise.wrapper(loyalty.api.loyaltyCards.updateAll)
    service.updateLoyaltyCards = cards => {
      return updateCards(cards).then(service.fetchLoyaltyCards)
    }

    // Loyalty Passes are used for adding to mobile wallet
    const getPasses = ngPromise.wrapper(loyalty.api.loyaltyPasses.getAll)
    service.fetchLoyaltyPasses = () => {
      service.loyaltyPassesPromise = getPasses().then(response => {
        service.loyaltyPasses = response.items || []
        return service.loyaltyPasses
      })

      return service.loyaltyPassesPromise
    }

    service.getLoyaltyPasses = () => service.loyaltyPassesPromise || service.fetchLoyaltyPasses()

    const createPass = ngPromise.wrapper(loyalty.api.loyaltyPasses.create)
    service.createLoyaltyPass = () => {
      return createPass()
    }

    // Returns the primary loyaltyCard for the currently authenticated user.
    service.getPrimaryLoyaltyCard = () => service.loyaltyCards.find(c => c.primary_card)

    service.getNonPrimaryLoyaltyCard = () => service.loyaltyCards.filter(c => !c.primary_card)
  })
