import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'
import * as userListSelectors from '@app/services/userList/selectors'
import * as userListThunks from '@app/services/userList/thunks'
import {
  findIndexOfItemInList,
  findItemInList,
  findItemsInList,
  findItemsInSomeList,
} from '@app/services/userList/utils'

export default angular
  .module('ListServicesModule', [])
  .service(
    'list',
    function list(
      $ngRedux,
      $q,
      $rootScope,
      auth,
      cartService,
      config,
      configurableProductService,
      errorModalService
    ) {
      const service = this
      service.lists = []

      const mapStateToService = state => {
        // Careful not to assign anything directly to service.lists or that will break all the pointers
        service.lists.length = 0
        userListSelectors.userListsSelector(state).forEach(list => {
          service.lists.push(angular.copy(list))
        })

        return {}
      }
      $ngRedux.connect(mapStateToService, {
        ...userListThunks,
      })(service)

      service.refresh = () => service.fetchUserLists()

      service.getList = (listId, requirePricing) =>
        service.fetchUserList({ listId, requirePricing })

      service.formatListItem = function (item, configuration) {
        if (item.store_product) {
          const wrapped = _.clone(item)
          wrapped.order_by_weight = item.store_product.order_by_weight
          return wrapped
        }
        let selectedConfiguration
        if (configuration) {
          selectedConfiguration = angular.copy(item.product_config)
          selectedConfiguration.options = configuration
        }

        return {
          item_type: 'store_product',
          store_product: item,
          order_by_weight: item.order_by_weight,
          quantity: item.ordering_parameters.min,
          product_config: selectedConfiguration || null,
        }
      }

      // If configuration is provided, we assume it is a selected configuration from the PDP and that items is therefore of length 1
      service.create = function (name, items, configuration) {
        const wrappedList = []

        if (items) {
          _.each(items, item => {
            wrappedList.push(service.formatListItem(item, configuration))
          })
        }

        if (name) {
          return service
            .createUserList({
              items: wrappedList,
              name,
            })
            .catch(response => {
              const errorMsg = response.data ? response.data.message : response.message
              return errorModalService.open(errorMsg, 'OK')
            })
        }
        return $q.reject()
      }

      service.remove = function (list) {
        const deferred = $q.defer()

        if (list) {
          errorModalService
            .open('Are you sure you want to delete this list?', 'OK', 'Cancel', 'Confirm')
            .then(() => {
              service.removeUserList(list).then(() => {
                deferred.resolve()
              })
            })
        }

        return deferred.promise
      }

      service.update = list => {
        const deferred = $q.defer()

        service.updateUserList(list).then(() => {
          deferred.resolve()
        })

        return deferred.promise
      }

      // If configuration is provided, we assume it's a selected configuration from the PDP
      service.itemInList = (item, list, configuration, shouldReturnIndex) => {
        let itemIndexInList
        let sanitizedSelectedItemConfiguration
        const itemProductId = service.formatListItem(item).store_product.id

        if (item.product_config && item.product_config.options) {
          if (configuration) {
            // We're checking to see if a selected configuration in the PDP is in the given list
            sanitizedSelectedItemConfiguration = configurableProductService.formatConfigForComparison(
              configuration
            )
          } else {
            // We're checking to see if an item (from the cart, from a list) is in the given list
            sanitizedSelectedItemConfiguration = configurableProductService.formatConfigForComparison(
              item.product_config.options
            )
          }

          itemIndexInList = _.findIndex(list.items, listItem => {
            if (listItem.product_config && listItem.product_config.options) {
              // eslint-disable-next-line eqeqeq
              const hasSameProductId = listItem.store_product.id == itemProductId
              const sanitizedListItemConfiguration = configurableProductService.formatConfigForComparison(
                listItem.product_config.options
              )
              return (
                hasSameProductId &&
                configurableProductService.areProductConfigsAndOptionsEqual(
                  sanitizedSelectedItemConfiguration,
                  sanitizedListItemConfiguration
                )
              )
            }
            return false
          })
        } else {
          itemIndexInList = _.findIndex(
            list.items,
            // eslint-disable-next-line eqeqeq
            listItem => listItem.store_product.id == itemProductId
          )
        }

        return shouldReturnIndex ? itemIndexInList : itemIndexInList !== -1
      }

      service.itemInList = (item, list, configuration, shouldReturnIndex) =>
        shouldReturnIndex
          ? findIndexOfItemInList(item, list, configuration)
          : findItemInList(item, list, configuration)

      // If configuration is provided, we assume it is a selected configuration from the PDP and that items is therefore of length 1
      service.itemsInList = (items, list, configuration) =>
        findItemsInList(items, list, configuration)

      // If configuration is provided, we assume it is a selected configuration from the PDP and that items is therefore of length 1
      service.itemsInOneList = (items, configuration) =>
        findItemsInSomeList(items, service.lists, configuration)

      const updateList = (currentList, updatedList) =>
        service
          .updateUserList(updatedList)
          .then(response => {
            currentList.items = angular.copy(response.payload.items)
          })
          .finally(() => {
            currentList.isSyncing = false
          })

      // If configuration is provided, we assume it is a selected configuration from the PDP and that items is therefore of length 1
      service.addItems = (items, list, configuration) => {
        list.isSyncing = true

        const listItemsToAdd = fp.flow(
          fp.filter(item => !service.itemInList(item, list, configuration)),
          fp.map(item => service.formatListItem(item, configuration))
        )(items)

        if (listItemsToAdd.length > 0) {
          const updatedList = angular.copy(list)
          updatedList.items = list.items.concat(listItemsToAdd)

          return updateList(list, updatedList)
        }
        list.isSyncing = false
        return $q.resolve()
      }

      // If configuration is provided, we assume it is a selected configuration from the PDP and that items is therefore of length 1
      service.removeItems = (items, list, configuration, requirePricing = true) => {
        list.isSyncing = true

        const updatedList = angular.copy(list)
        updatedList.items = angular.copy(list.items)

        let hasItemsToRemove = false
        _.each(items, itemToRemove => {
          const itemToRemoveIndexInList = service.itemInList(
            itemToRemove,
            list,
            configuration,
            true
          )

          if (itemToRemoveIndexInList !== -1) {
            updatedList.items[itemToRemoveIndexInList].quantity = 0
            hasItemsToRemove = true
          }
        })

        if (hasItemsToRemove) {
          return updateList(list, { ...updatedList, require_pricing: requirePricing })
        }
        list.isSyncing = false
        return $q.resolve()
      }

      service.loadListToCart = cartService.loadListToCart

      $rootScope.$on('auth', (event, action) => {
        if (action === 'userLoggedIn') {
          service.refresh()
        }
      })
    }
  )
