/**
 * @deprecated This is a util for use in the angular app to fetch the height of
 * the sticky header in react. Do not use in the react app
 */
export const getStickyHeaderHeight = (inEComLightMode = false) => {
  const stickyHeaderHeight = document.getElementById('sticky-react-header')?.offsetHeight
  return inEComLightMode ? 72 : stickyHeaderHeight ?? 113
}
