import React, { useState, useCallback } from 'react'
import PropTypes from 'prop-types'
import dayjs from 'dayjs'
import { useTimeslotsQuery } from '@instacart/enterprise-services-hooks'
import { createStyles, FormLabel, GridContainer, Select } from '@instacart/cocktail'
import { lang as langService, utils as utilsService } from '@instacart/enterprise-services'

import InputGroup from './InputGroup'

const DATE_FORMAT = 'YYYY/MM/DD'
const API_DATE_FORMAT = 'YYYY-MM-DD'

const lang = {
  timeslotDateLabel: '{{fulfillmentType}} date',
  timeslotTimeLabel: '{{fulfillmentType}} time',
  timeslotTimeOptionLabel: '{{from}} until {{to}}',
}

const useStyles = createStyles(() => ({
  labels: {
    textTransform: 'capitalize',
  },
}))

const getTimeslotOptions = ({ timeslots, date, options }) => {
  const uniqueDays = [...new Set(timeslots.map(timeslot => timeslot.fulfillment_date))]

  const days = uniqueDays.map(timeslot => ({
    label: dayjs(timeslot, API_DATE_FORMAT).format(options.dateFormat),
    value: timeslot,
  }))

  const filteredTimeslots = date
    ? timeslots.filter(timeslot => timeslot.fulfillment_date === date.value)
    : timeslots

  const times = filteredTimeslots.map(({ timeslot }) => {
    return {
      value: timeslot.id || timeslots.ext_id,
      label: langService.interpolate(lang.timeslotTimeOptionLabel, {
        from: timeslot.from_time,
        to: timeslot.to_time,
      }),
      ...timeslot,
    }
  })

  return { days, timeslots: times }
}

const TimeslotSelect = ({ query, type, dateFormat, onChange, inputSize }) => {
  const styles = useStyles()
  const [selectedDate, setSelectedDate] = useState(null)
  const [selectedTimeslot, setSelectedTimeslot] = useState(null)
  const { data } = useTimeslotsQuery(type, query)
  const handleTimeslotChange = timeslot => {
    setSelectedTimeslot(timeslot)
    onChange(timeslot)
  }
  const handleDateChange = date => {
    setSelectedDate(date)
    setSelectedTimeslot(null)
  }

  const memoizedTimeslotOptions = useCallback(
    () =>
      getTimeslotOptions({
        date: selectedDate,
        timeslots: data.items,
        options: { dateFormat },
      }),
    [data.items, selectedDate, dateFormat]
  )

  const { days, timeslots } = memoizedTimeslotOptions()

  return (
    <GridContainer data-test="timeslot-select-container">
      <InputGroup>
        <FormLabel css={[styles.labels]}>
          {langService.interpolate(lang.timeslotDateLabel, {
            fulfillmentType: query.fulfillment_type,
          })}
        </FormLabel>
        <Select
          inputSize={inputSize}
          data-test="timeslots-select-date"
          onChange={handleDateChange}
          options={days}
        />
      </InputGroup>

      <InputGroup>
        <FormLabel css={[styles.labels]}>
          {langService.interpolate(lang.timeslotTimeLabel, {
            fulfillmentType: query.fulfillment_type,
          })}
        </FormLabel>
        <Select
          inputSize={inputSize}
          native={utilsService.isMobile()}
          data-test="timeslots-select-time"
          disabled={!selectedDate}
          value={selectedTimeslot}
          onChange={handleTimeslotChange}
          options={timeslots}
        />
      </InputGroup>
    </GridContainer>
  )
}

const groceryQuery = PropTypes.shape({
  above_threshold: PropTypes.bool,
  cart_id: PropTypes.number,
  filter_product_blackouts_timeslots: PropTypes.bool,
  fulfillment_type: PropTypes.oneOf(['pickup', 'delivery']),
  user_timezone: PropTypes.isRequired,
})

const cateringQuery = PropTypes.shape({
  store_id: PropTypes.number.isRequired,
  fulfillment_type: PropTypes.oneOf(['pickup', 'delivery']),
  shopping_mode: PropTypes.oneOf(['catering', 'grocery']),
  user_timezone: PropTypes.string.isRequired,
})

TimeslotSelect.propTypes = {
  dateFormat: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  query: PropTypes.oneOfType([groceryQuery, cateringQuery]),
  inputSize: PropTypes.oneOf(['small', 'regular']),
  type: PropTypes.oneOf(['catering', 'grocery']),
}

TimeslotSelect.defaultProps = {
  inputSize: 'regular',
  dateFormat: DATE_FORMAT,
}

export default TimeslotSelect
