import { useState, useEffect, useCallback } from 'react'
import { loading, subscriptions } from '@instacart/enterprise-services'

const useForceUpdate = () => {
  const [, setTick] = useState(0)
  const update = useCallback(() => {
    setTick(tick => tick + 1)
  }, [])
  return update
}

export const useLoading = () => {
  const forceUpdate = useForceUpdate()
  useEffect(() => loading.store.subscribe(forceUpdate), [forceUpdate])
  return loading
}

export const useSubscriptions = () => {
  const forceUpdate = useForceUpdate()
  useEffect(() => subscriptions.store.subscribe(forceUpdate), [forceUpdate])
  return subscriptions
}
