import isCapacitor from '@app/utils/isCapacitor'
import { codePush } from 'capacitor-codepush'

export function getCodePushBundleLabel(cb: (id: string) => void) {
  // We are not releasing this on Cordova going forward.
  if (isCapacitor()) {
    codePush.getCurrentPackage().then(pack => cb(pack ? pack.label : ''))
  }
}
