import angular from 'angular'

export default angular
  .module('LoyaltyComponentModule', [])

  .config(($stateProvider, configConstants, langProvider) => {
    $stateProvider.state('main.account.loyalty', {
      // The ?enterPromoCode is here to handle opening the PromoCode modal.
      url: `${configConstants.loyaltyVanityUrl || '/loyalty'}?enterPromoCode`,
      views: {
        'content@main.account': {
          // The controller is included in the template
          template: require('./loyalty.html'),
          controllerAs: '$ctrl',
          controller(loyaltyOptIn) {
            // onEnter doesn't re-trigger on state reload.
            this.loyaltyOptIn = loyaltyOptIn
          },
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "loyaltyController", webpackPrefetch: true */ './loyaltyController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        loyaltyOptIn(config, userService) {
          return (
            config.loyalty.enableLoyaltyOptIn.loyaltyPage &&
            userService.fetchLoyaltyCards().then(() => userService.loyaltyCards.length === 0)
          )
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.loyaltyCard)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.loyaltyCard,
          },
        ],
        section: 'loyalty',
      },
    })
  })
