import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('couponsCategoriesServiceComponentModule', [])
  .service(
    'couponsCategoriesService',
    function couponsCategoriesService($q, $rootScope, Restangular) {
      const service = this

      $rootScope.$on('reloadState', () => {
        delete service.categories
      })

      service.fetch = function (refresh) {
        const deferred = $q.defer()

        if (!refresh && service.categories) {
          deferred.resolve(service.categories)
          return deferred.promise
        }

        Restangular.all('offers/categories')
          .getList({ type: 'coupon' })
          .then(categories => {
            service.categories = categories

            _.each(categories, category => {
              // Pre-resolve tree information for all categories

              category.state = 'main.shop.coupons.category'
              category.params = { category_id: category.id }
              category.tags = [
                {
                  name: 'Clipped',
                  value: 'clipped',
                },
              ]
            })

            deferred.resolve(categories)
          })

        return deferred.promise
      }

      service.rootCategories = function () {
        return _.filter(service.categories, category => {
          return !category.parent_id
        })
      }
    }
  )
