// Helper for a11y tabs such that they can be controlled
// properly by keyboard users using arrow keys

import angular from 'angular'

export default angular
  .module('TabsKeyboardControlModule', [])
  .directive('tabsKeyboardControl', () => ({
    link: (scope, el) => {
      const KEYS = {
        left: 37,
        right: 39,
      }

      const setTabIndex = () => {
        el[0].querySelectorAll('button[aria-selected="false"]').forEach(unselectedButton => {
          // give unselected buttons tabindex=-1 so user cannot tab into them
          unselectedButton.setAttribute('tabindex', -1)
        })

        // remove tabindex from selected tab so it can be entered with the tab key
        el[0].querySelector('button[aria-selected="true"]').removeAttribute('tabindex')
      }

      el.on('keydown', e => {
        const keyPress = e.keyCode

        setTabIndex()

        switch (keyPress) {
          case KEYS.left:
            e.preventDefault()
            e.target.previousElementSibling.focus()
            break
          case KEYS.right:
            e.preventDefault()
            e.target.nextElementSibling.focus()
            break
        }
      })
    },
  }))
