import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'
import dayjs from 'dayjs'

export default angular
  .module('TimeslotControllerModule', [])

  .controller(
    'TimeslotController',
    function (
      $scope,
      $state,
      Restangular,
      cartService,
      compareCartService,
      config,
      reservationService,
      timeslots
    ) {
      $scope.cartService = cartService
      $scope.reservationService = reservationService
      $scope.selectedTimeslot = null

      reservationService.reservation = null

      // Find the first and the last day in our data set

      let lastDay
      let firstDay

      _.each(
        _.map(timeslots, timeslot => {
          return dayjs(timeslot.fulfillment_date).startOf('day')
        }),
        day => {
          if (!lastDay || day.unix() > lastDay.unix()) {
            lastDay = day
          }

          if (!firstDay || day.unix() < firstDay.unix()) {
            firstDay = day
          }
        }
      )

      // Sort timeslots

      timeslots = _.sortBy(timeslots, timeslot => {
        return timeslot.timeslot.from_time
      })

      // Generate a list of days grouped by week

      $scope.days = []

      for (let day = dayjs(firstDay); day.diff(lastDay, 'days') <= 0; day = day.add(1, 'days')) {
        let dayOfWeek = day.format('dddd')
        const diff = dayjs().startOf('day').diff(day, 'days')

        // Today

        if (!diff) {
          dayOfWeek = 'Today'
        }

        // Tomorrow

        if (diff === -1) {
          dayOfWeek = 'Tomorrow'
        }

        $scope.days.push({
          dayOfWeek,
          dayOfMonth: day.format('D'),
          month: day.format('MMMM'),
          date: day.format('MM-DD-YYYY'),
          daysSinceStart: day.diff(firstDay, 'days'),
        })
      }

      // Group days by week

      $scope.daysByWeek = fp.flow(
        fp.groupBy(day => Math.floor(day.daysSinceStart / 7)),
        fp.toArray
      )($scope.days)

      // Group timeslots by day

      $scope.timeslotsByDay = _.groupBy(timeslots, timeslot => {
        return dayjs(timeslot.fulfillment_date).format('MM-DD-YYYY')
      })

      $scope.currentWeek = 0 // Keep track of which week we're viewing

      // Select a particlar day and the first timeslot for that day in the select list

      $scope.selectDay = function (day) {
        $scope.selectedDay = day
        $scope.selectedTimeslot = null
      }

      // Select the first day by default

      $scope.selectDay($scope.days[0])

      // Reserve the selected timeslot - Check if inventory or price of products have changed.

      $scope.reserve = function () {
        delete $scope.error
        const timeslotStartTime = `${$scope.selectedTimeslot.fulfillment_date} ${$scope.selectedTimeslot.timeslot.from_time}`
        compareCartService
          .compareCart(true, 'timeslot', {
            timeslot: $scope.selectedTimeslot.timeslot,
            fulfillment_date: timeslotStartTime,
          })
          .then(() => {
            processReserve()
          })
      }

      function processReserve() {
        reservationService.makeReservation($scope.selectedTimeslot.plain()).then(
          () => {
            cartService.fetch() // Update the cart so fees are reflected

            $state.go('^.confirmation')
          },
          response => {
            $scope.error = response.data ? response.data.message : response.message
          }
        )
      }
    }
  )
