export const formatCurrency = (amount: string | number) => {
  if (!amount || Number.isNaN(amount)) {
    return ''
  }

  let formattedAmount

  if (typeof amount === 'string') {
    formattedAmount = Number(amount)
  } else {
    formattedAmount = amount
  }

  return `$${formattedAmount.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}`
}
