import angular from 'angular'

export default angular
  .module('FocusFirstInputComponentModule', [])
  .directive('focusFirstInput', function () {
    return {
      restrict: 'A',
      link: (scope, el) => {
        // Use evalAsync in case first input is wrapped by ng-if
        scope.$evalAsync(() => {
          const inputs = el.find('input')
          if (inputs.length) {
            inputs[0].focus()
          }
        })
      },
    }
  })
