import angular from 'angular'
import template from './priceAlertButton.html'

export default angular
  .module('PriceAlertButtonComponentModule', [])
  .directive('priceAlert', function () {
    return {
      scope: {
        product: '=',
        small: '=',
        source: '@',
      },
      template,
      controller($rootScope, $scope, analytics, auth, config, lang, priceAlertsService) {
        $scope.auth = auth
        $scope.config = config
        $scope.lang = lang

        $scope.togglePriceAlert = () => {
          analytics.logSegmentEvent('store.price_alert', {
            product_details: $scope.product,
            add_price_alert: !$scope.product.price_alert,
            remove_price_alert: !!$scope.product.price_alert,
            price_alert_source: $scope.source,
          })
          return priceAlertsService.togglePriceAlert($scope.product)
        }
      },
    }
  })
