import angular from 'angular'

import { SHOPPING_SELECTOR_STAGES } from '@app/angular/constants/shoppingSelectorStages'
import { SHOPPING_MODES } from '@app/angular/constants/context'

import template from './emptyResultsContextSwitcher.html'

export default angular
  .module('EmptyResultsContextSwitcherModule', [])
  .component('emptyResultsContextSwitcher', {
    bindings: {
      searchTerm: '@',
      showPickupContextSwitch: '<',
      showDeliveryContextSwitch: '<',
      showCateringContextSwitch: '<',
    },
    template,
    controller(
      $rootScope,
      $scope,
      auth,
      cartService,
      cartListTransferModalService,
      cartListTransferService,
      config,
      lang,
      shoppingSelectorService,
      shoppingContextService
    ) {
      $scope.lang = lang
      $scope.showPickupContextSwitch = this.showPickupContextSwitch
      $scope.showDeliveryContextSwitch = this.showDeliveryContextSwitch
      $scope.showCateringContextSwitch = this.showCateringContextSwitch

      const currentContext = auth.user.context.intent
      if (currentContext) {
        $scope.noItemsInContextHeader = lang.template(lang.values.noItemsInContextHeader, {
          term: this.searchTerm,
          context: config.shoppingSelector[currentContext].title,
        })
        $scope.noItemsInContextBody = lang.template(lang.values.noItemsInContextBody, {
          context: config.shoppingSelector[currentContext].title,
        })
      }

      $scope.switchShoppingMethodToDelivery = () => {
        auth.setIntent(SHOPPING_MODES.DELIVERY).then(() => {
          shoppingSelectorService
            .open(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
            .finally($rootScope.reloadState)
        })
      }

      $scope.switchShoppingMethodToPickup = () => {
        auth.setIntent(SHOPPING_MODES.PICKUP).then(() => {
          shoppingSelectorService
            .open(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
            .finally($rootScope.reloadState)
        })
      }

      $scope.switchShoppingMethodToCatering = () => {
        shoppingContextService.setSelectedFulfillmentType(SHOPPING_MODES.CATERING)

        const updateContext = () =>
          auth.setIntent(SHOPPING_MODES.CATERING).then(() => {
            shoppingSelectorService
              .open(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
              .finally($rootScope.reloadState)
          })
        const shoppingMode = auth.getCurrentShoppingMode()
        const initialContext = config.shoppingSelector[shoppingMode].title
        const updatedContext = config.shoppingSelector[SHOPPING_MODES.CATERING].title
        const itemCount = cartService.countItems()

        cartListTransferModalService
          .open({ initialContext, updatedContext, itemCount })
          .then(updateContext)
      }
    },
  })
