import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'

import template from './pickupForm.html'

const PICKUP_FORM_ID = 'pickup'

class PickupForm {
  constructor(
    $q,
    $scope,
    $timeout,
    api,
    auth,
    checkoutAnalytics,
    config,
    lang,
    shoppingSelectorService,
    storesService,
    utilService
  ) {
    const {
      intent,
      user: { store },
    } = auth

    Object.assign(this, {
      $q,
      $scope,
      $timeout,
      api,
      auth,
      checkoutAnalytics,
      config,
      intent,
      lang,
      searchTerm: '',
      shoppingSelectorService,
      storesService,
      utilService,
    })

    this.selectedStore = store
  }

  $onInit() {
    const { $scope } = this

    this.getInitialLocation().then(location => this.setCurrentLocation(location))
    this.filteredStores = this.stores
    // Executes whenever the *stores* prop changes
    $scope.$watch(
      () => this.stores,
      () => this._handleStoresChange()
    )

    $scope.$watch(
      () => this.editMode,
      () => {
        if (this.editMode) {
          this.enterEditMode()
        }
      }
    )
  }

  /**
   * Sets the current location to the first store in the list
   * in case there's no selected store
   *
   * @returns { undefined }
   */
  _handleStoresChange() {
    if (!this.selectedStore && this.stores.length > 0) {
      this.currentLocation = {
        ...this.stores[0].location,
      }
    }
    this.filteredStores = this.stores
  }

  /**
   * Returns promise that resolve in the initial map location based on the user store
   * or the user geolocation
   *
   * @returns { Promise }
   */
  getInitialLocation() {
    const deferred = this.$q.defer()

    if (this.selectedStore) {
      deferred.resolve(this.selectedStore.location)
    } else if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {
        const { latitude, longitude } = position.coords

        deferred.resolve({ latitude, longitude })
      })
    }

    return deferred.promise
  }

  /**
   * Sets the current location
   *
   * @param { object } location
   */
  setCurrentLocation(location) {
    this.currentLocation = location
  }

  /**
   * Searches for an address and sets the current location
   *
   * @returns { undefined }
   */
  searchAddress() {
    this.api
      .getGeocodeAddress(this.searchTerm)
      .then(response => this.handleAddressSearchSuccess(response))
      .then(response => this.filterList(response))
      .finally(() => (this.searchTerm = ''))
  }

  filterList(location) {
    if (!location) return

    const { stores, matchedStore } = this.storesService.filterStores({
      location,
      allStores: this.stores,
      searchTerm: this.searchTerm,
      intent: SHOPPING_MODES.PICKUP,
      storeID: this.auth.getContext().storeNumber,
    })

    this.filteredStores = stores
    this.currentLocation = {
      latitude: matchedStore.location.latitude,
      longitude: matchedStore.location.longitude,
    }
    this.scrollToStore(matchedStore)
  }

  /**
   * Handles the getGeocodeAddress success response
   *
   * @param { object } response
   */
  handleAddressSearchSuccess(response) {
    const { features } = response.data

    if (features && features.length > 0) {
      const [longitude, latitude] = features[0].center

      this.currentLocation = {
        latitude,
        longitude,
      }
    }

    return this.currentLocation
  }

  /**
   * Changes the section to edit mode by calling the
   * edit prop function
   *
   * @returns { undefined }
   */
  enterEditMode() {
    this.edit({ step: PICKUP_FORM_ID })

    this.selectedStore = this.auth.user.store
    if (this.selectedStore) {
      this.$timeout(() => this.scrollToStore(this.selectedStore))
    }

    this.checkoutAnalytics.viewFulfillmentOptions()
  }

  /**
   * Sets the store selected by the user
   *
   * @param { object } store
   * @returns { undefined }
   */
  selectStore(store) {
    this.$timeout(() => {
      this.selectedStore = store
      this.currentLocation = store.location
    })
  }

  /**
   * Handles the store selection through the map
   *
   * @param { object } store
   * @returns { undefined }
   */
  handleMapStoreSelect(store) {
    this.selectStore(store)
    this.scrollToStore(store)
  }

  /**
   * Scrolls the list to a given store
   *
   * @param { object } store
   * @returns { undefined }
   */
  scrollToStore(store) {
    this.$timeout(() => {
      const storeEl = document.querySelector(`#unata-store-list-item-${store.id}`)
      const parentEl = document.querySelector('.unata-store-list')
      const height = storeEl.offsetTop

      parentEl.scrollTo(0, height)
    })
  }

  /**
   * Updates the user current store
   * and calls the prop method *onStoreApplied*
   * so the parent can handle it
   *
   * @param { object } store
   * @returns { Promise }
   */
  applyStore(store) {
    const { user } = this.auth
    const hasChanged = user.store.id !== store.id || !this.auth.user.has_changed_store

    this.onStoreApplied({ hasChanged, store })
  }
}

export default angular.module('PickupFormModule', []).component('pickupForm', {
  bindings: {
    edit: '&',
    editMode: '<',
    onEditModeChange: '&',
    onStoreApplied: '&',
    title: '@',
    stores: '<',
    hideChevron: '<',
    userStore: '<',
  },
  template,
  controller: PickupForm,
})
