import angular from 'angular'

export default angular
  .module('FacetedFilterModalLineItemModule', [])
  .component('facetedFilterModalLineItem', {
    bindings: {
      tagOption: '<',
      selectedCheckboxObject: '<',
      toggleOption: '&',
    },
    controller(tagsService) {
      const vm = this
      vm.tagValue = vm.tagOption.id || vm.tagOption.name
      vm.fullTag = tagsService.byVal(vm.tagValue, 'filters')
      // tagOption.name needs to be set to lower case to be used for label and input value
      vm.displayName = vm.tagOption.display_name || vm.tagOption.name.toLowerCase()
      vm.itemCount = vm.tagOption.item_count

      vm.onChange = () => {
        vm.toggleOption({ id: vm.tagValue })
      }
    },
    template: `
      <div data-test="facet {{::$ctrl.tagValue}}">
        <input type="checkbox" id="tag-{{::$ctrl.tagValue}}" name="tag-{{::$ctrl.tagValue}}" ng-click="$ctrl.onChange()" ng-model="$ctrl.selectedCheckboxObject[$ctrl.tagValue]">
        <label for="tag-{{::$ctrl.tagValue}}">
          <div ng-if="::$ctrl.fullTag.icon" class="tag-icon" tag-style tag="$ctrl.fullTag" tag-location="filters" aria-hidden="true"></div>
          <span class="capitalize">{{::$ctrl.displayName}} {{::$ctrl.itemCount ? '(' + $ctrl.itemCount + ')' : '' }}</span>
          <span class="indicator" aria-hidden="true"></span>
        </label>
      </div>
    `,
  })
