// Override the default ui-bootstrap modal template to add closing when
// clicking the container

import angular from 'angular'

export default angular
  .module('uib/template/modal/window.html', [])
  .run([
    '$templateCache',
    'lang',
    $templateCache =>
      $templateCache.put('uib/template/modal/window.html', require('./modalTemplate.html')),
  ])
