import angular from 'angular'
import template from './errorModal.html'

export default angular
  .module('errorModalServiceModule', [])

  // Service to provide access to launching the modal dialog

  .service('errorModalService', function errorModalService($uibModal, uibModalAriaLabelService) {
    this.open = function (
      message,
      okText,
      cancelText,
      title,
      returnModal,
      meta = {},
      callbacks = {}
    ) {
      const modal = $uibModal.open({
        template,
        controller: [
          '$scope',
          'analytics',
          'config',
          function ($scope, analytics, config) {
            Object.assign($scope, {
              message,
              okText,
              cancelText,
              title,
              meta,
              config,
              ...callbacks,
            })

            $scope.handleClose = () => {
              analytics.logEvent('error modal ok button', 'click')
              if ($scope.onClose) {
                $scope.onClose()
              }
              $scope.$close()
            }

            $scope.handleDismiss = () => {
              analytics.logEvent('error modal cancel button', 'click')
              if ($scope.onDismiss) {
                $scope.onDismiss()
              }
              $scope.$dismiss()
            }
          },
        ],
        resolve: {
          message: () => message,
          okText: () => okText,
          cancelText: () => cancelText,
          title: () => title,
          meta: () => meta,
          ariaLabel: () => {
            uibModalAriaLabelService.setLabel(title)
          },
        },
        keyboard: true,
        backdrop: 'static',
        windowClass: 'no-scroll',
      })

      // Allow returning the actual modal instance, or by default
      // just return the result promise

      if (returnModal) {
        return modal
      }
      return modal.result
    }

    /**
     * Opens a custom modal for a given *customTemplate*
     *
     * @param { String } customTemplate - Modal template
     * @param { Object } props - attributes that will be assigned to the modal controller instance.
     *                           props will be available in the template via `$ctrl`
     */
    this.openCustom = (customTemplate, props = {}) => {
      const modalInstance = $uibModal.open({
        template: customTemplate,
        controller: [
          '$interpolate',
          '$scope',
          'lang',
          function ($interpolate, $scope, lang) {
            let isRendered = false

            function callAndShutModalFactory(fn, isDismiss = false) {
              if (!fn._wrappedShut) {
                fn._wrappedShut = (...args) => {
                  const result = fn(...args)
                  if (isDismiss) {
                    $scope.$dismiss()
                  } else {
                    $scope.$close()
                  }
                  return result
                }
              }

              return fn._wrappedShut
            }

            Object.assign(this, {
              ...props,
              lang,
              /**
               * You can access helper functions in the template via $ctrl.$custom...
               */
              $custom: {
                /**
                 * Factory method: Returns a function that calls the passed function and then closes
                 * the modal. It is cached (using the original function object as a cache) to
                 * prevent unnecessary re-rendering. This can be used for React components that
                 * accept one function argument being passed in.
                 *
                 * @example:
                 *   <react-error-window
                 *     on-click-primary-button="($ctrl.$custom.callAndCloseFactory($ctrl.myFn))"
                 *   />
                 *
                 * @param fn
                 * @returns {function(...[*]): *}
                 */
                callAndCloseFactory: callAndShutModalFactory,
                callAndDismissFactory: fn => callAndShutModalFactory(fn, true),
                /**
                 * Turns a template string into a interpolated string.
                 * Use this for passing customized strings to React components.
                 * Don't use template literals (i.e. backticks `etc`), due to browser compatibility,
                 * you'll have to use escaped quotes.
                 *
                 * @example
                 *   <react-error-window
                 *     body="$ctrl.$custom.templateToString('
                 *       {{::$ctrl.lang.values.cancelOrderModal.pleaseDontCancel}}
                 *     ')"
                 *   />
                 *
                 * @param template
                 * @returns {*}
                 */
                templateToString: template => $interpolate(template)({ $ctrl: this }),
                isModalRendered: () => isRendered,
              },
            })

            modalInstance.opened.then(() => (isRendered = true)).catch(() => (isRendered = false))
            modalInstance.$dismiss = $scope.$dismiss
          },
        ],
        keyboard: true,
        backdrop: 'static',
        controllerAs: '$ctrl',
      })

      return modalInstance
    }
  })
