import angular from 'angular'

import appendMetaTagDescription from '@app/utils/appendMetaTagDescription'
import { appendRecipeStruturedData } from '@app/utils/appendStructuredData'
import { deleteJsonLd } from '@app/utils/jsonLd'
import RecipeCategoriesServiceModule from './recipeCategoriesService'
import RecipeDetailsModalServiceModule from './recipeDetailsModalService'
import RecipeIngredientsModule from './recipeIngredients'
import RecipeMetaModule from './recipeMeta'
import RecipeProductsModule from './recipeProducts'
import SavedRecipesCellModule from './savedRecipesCell'

export default angular
  .module('RecipesComponentModule', [
    RecipeCategoriesServiceModule.name,
    RecipeDetailsModalServiceModule.name,
    RecipeIngredientsModule.name,
    RecipeMetaModule.name,
    RecipeProductsModule.name,
    SavedRecipesCellModule.name,
  ])
  .config(($stateProvider, langProvider) => {
    const recipesTreeStates = {
      recipesHome: {
        name: langProvider.values.recipes,
        state: 'main.shop.recipes',
        // sort: true
      },
    }

    $stateProvider
      .state('main.recipes', {
        url: '/recipes',
        abstract: true,
      })
      .state('main.recipes.details', {
        url: '/{recipeId}/{name}?categoryIds',
        views: {
          'content@': {
            template: require('./recipeDetails.html'),
            controller: 'RecipeDetailsController',
            controllerAs: 'recipeDetails',
          },
        },
        params: {
          name: { squash: true, value: null },
          index: null,
        },
        resolve: {
          loadCtrl: $ocLazyLoad => {
            return import(
              /* webpackChunkName: "recipeDetailsController", webpackPrefetch: true */ './recipeDetailsController'
            )
              .then(mod => $ocLazyLoad.load(mod.default))
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },
          recipe($stateParams, Restangular) {
            return Restangular.one('recipes', $stateParams.recipeId).get()
          },
          savedRecipes(savedRecipesService) {
            return savedRecipesService.getSavedRecipes().then(savedRecipesList => {
              return savedRecipesList
            })
          },
        },
        onEnter: recipe => {
          appendRecipeStruturedData(recipe, langProvider.values.clientName)
          appendMetaTagDescription(recipe.description)
        },
        onExit: () => {
          deleteJsonLd()
        },
        data: {
          recipesHomeState: recipesTreeStates.recipesHome,
          setPageTitleAndBreadcrumbs: true,
        },
      })
  })
