import angular from 'angular'
import template from './barcodeNotFound.html'
import BarcodeScannerButtonModule from './barcodeScanner'

export default angular
  .module('BarCodeComponentModule', [BarcodeScannerButtonModule.name])

  .config($stateProvider => {
    $stateProvider.state('main.barcodeNotFound', {
      url: '/scan-not-found',
      views: {
        'content@': {
          template,
        },
      },
    })

    $stateProvider.state('main.scanBarcode', {
      url: '/scan-barcode',
      views: {
        'content@': {
          controller($state, $timeout, barcodeScanner) {
            barcodeScanner.search()
          },
        },
      },
    })
  })
