import angular from 'angular'
import { AUTH_TYPES, PAYMENT_TYPES } from '@app/angular/constants/payments'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import template from './stripePaymentForm.html'

const RENDER_DELAY = 200

export default angular
  .module('StripePaymentFormComponentModule', [])

  .component('stripePaymentForm', {
    bindings: {
      additionalFormInfo: '@?',
      addCardLabel: '@?',
      enableGiftCards: '<',
      showCreditCardSelectButton: '<?',
      hideTitle: '<?',
      useIcTheme: '<?',
      authType: '<?', // auth_capture | external_auth
      styleAddCardAsLink: '<?',
      createCreditCardLabel: '@?',
      enableCancelFormButton: '<?',
      enableCreditCardListBackground: '<?',
      enableCreditCardSelectButton: '<?',
      onCreditCardSelect: '&?',
      onCancelFormClick: '&?',
      fullWidthButtons: '<?',
      squaredButtons: '<?',
      enableFormBackground: '<?',
      enableCreditCardSelectAfterAdd: '<?',
      requestUserPaymentOnFirstFetch: '<?',
      showSaveCreditCardCheckbox: '<?',
    },
    controller(
      $scope,
      $timeout,
      confirmationModal,
      giftCardModalService,
      lang,
      mobileService,
      Restangular,
      stripe,
      stripePayment,
      errorModalService
    ) {
      const vm = this
      const onShowCardForm = $scope.$parent?.onShowCardForm
      const checkoutNewCreditCard = $scope.$parent?.checkoutNewCreditCard

      vm.lang = lang
      vm.isMobile = mobileService.isMobile
      vm.authType = vm.authType || AUTH_TYPES.CAPTURE
      vm.checkoutSave = $scope.$parent?.checkoutSave
      vm.paymentContext = $scope.$parent?.paymentContext

      const deletePaymentInstrument = id => {
        return Restangular.one('user')
          .one('payment', id)
          .remove({ service_type: vm.paymentContext })
      }

      vm.handleDeleteClick = creditCard => {
        const message = vm.lang.template(vm.lang.values.deleteCreditCardMessage, {
          digits: creditCard.last_four_digits,
        })

        confirmationModal.open({ message }).then(proceed => {
          if (proceed) {
            deletePaymentInstrument(creditCard.id)
              .then(() => {
                vm.paymentInstruments = vm.paymentInstruments.filter(pi => pi.id !== creditCard.id)
                if (!vm.paymentInstruments.length) {
                  vm.showStripeForm()
                }
              })
              .catch(() => {
                const { defaultErrorMessage, ok } = vm.lang.values

                errorModalService.open(defaultErrorMessage, ok)
              })
          }
        })
      }

      /**
       * Shows the stripe form
       * @returns { undefined }
       */
      vm.showStripeForm = () => {
        vm.shouldShowStripeForm = true

        if ($scope.$parent?.toggleIsShowingAddCard) {
          // eslint-disable-next-line no-unused-expressions
          $scope.$parent?.toggleIsShowingAddCard(true)
        }

        if (onShowCardForm) {
          onShowCardForm()
        }
      }

      /**
       * Opens a gift cards modal
       *
       * @returns { undefined }
       */
      vm.openGiftCardsModal = () => {
        const { payment } = $scope.$parent

        giftCardModalService.open({ payment })
      }

      /**
       * Hides the stripe form
       * @returns { undefined }
       */
      vm.hideStripeForm = () => {
        vm.shouldShowStripeForm = false

        if ($scope.$parent?.toggleIsShowingAddCard) {
          // eslint-disable-next-line no-unused-expressions
          $scope.$parent?.toggleIsShowingAddCard(false)
        }
      }

      /**
       * Fetch and set user
       */
      const fetchPaymentInstruments = existingExtId =>
        Restangular.one('user')
          .one('payment')
          .get({
            payment_type: vm.authType,
            service_type: vm.paymentContext,
          })
          .then(response => {
            // Save payment instruments and show or hide Stripe form as appropriate
            const instruments = response[vm.authType].payment_instruments.filter(pi => pi.id)
            const firstInstrument = instruments[0]

            vm.paymentInstruments = instruments.sort((a, b) => a.source.localeCompare(b.source))

            stripePayment.status.loaded = true

            if (vm.paymentInstruments.length) {
              vm.hideStripeForm()

              // Selects the last used or first credit card if there's no credit card selected
              if (!vm.selectedCard) {
                vm.selectedCard =
                  (existingExtId && instruments.find(({ ext_id }) => ext_id === existingExtId)) ||
                  firstInstrument
              }

              if (vm.enableCreditCardSelectAfterAdd) {
                $timeout(vm.setCreditCard)
              }
            } else {
              // BE says customer has no payment instruments / previously added credit cards
              vm.showStripeForm()
            }

            return vm.paymentInstruments
          })

      $scope.$watch('$ctrl.selectedCard', () => {
        // Allow user to proceed only if a payment instrument has been selected
        const hasSelectedCard = !!vm.selectedCard

        if (hasSelectedCard) {
          // $scope.$parent.payment lives in the CheckoutPaymentController
          if ($scope.$parent.payment) {
            $scope.$parent.payment[vm.authType] = { payment_instrument: vm.selectedCard }
          }
        }
        stripePayment.status.ready = hasSelectedCard
      })

      /**
       * Handles the cancel click event
       * @returns { undefined }
       */
      vm.handleCancelClick = () => {
        const { paymentInstruments } = vm

        if (paymentInstruments.length > 0) {
          vm.hideStripeForm()
          return
        }

        if (paymentInstruments.length === 0 && vm.enableCancelFormButton) {
          vm.onCancelFormClick()
        }
      }

      /**
       * Executes when a credit card is successfully added
       * @param { object } card
       * @returns { undefined }
       */
      vm.handleCreditCardAdd = card => {
        vm.hideStripeForm()
        vm.paymentInstruments.push(card)
        vm.selectedCard = card

        if ($scope.$parent.payment) {
          $scope.$parent.payment[vm.authType] = { payment_instrument: card }
        }

        if (vm.enableCreditCardSelectAfterAdd) {
          $timeout(vm.setCreditCard, RENDER_DELAY)

          if (checkoutNewCreditCard) {
            checkoutNewCreditCard(true)
          }
        }
      }

      /**
       * Executes when a credit card add call fails
       * @returns { undefined }
       */
      vm.handleCreditCardAddError = (e = {}) => {
        let errorMessage = e.data?.message || e?.message || vm.lang.values.defaultErrorMessage
        if (e.status === HTTP_ERRORS.TOO_MANY_REQUESTS) {
          errorMessage = lang.values.tooManyRetriesError
        }
        errorModalService.open(errorMessage, vm.lang.values.ok)

        if (checkoutNewCreditCard) {
          checkoutNewCreditCard(false)
        }
      }

      vm.setCreditCard = creditCard => {
        vm.onCreditCardSelect && vm.onCreditCardSelect({ creditCard })
      }

      // Load payment instruments
      const firstFetch = () => {
        const paymentExtIdOfCurrentAuthType = $scope.$parent?.payment?.[vm.authType]?.ext_id
        const paymentExtIdOfExternalAuthType =
          $scope.$parent?.payment?.[PAYMENT_TYPES.EXTERNAL_AUTH]?.ext_id

        // If the payment id isn't found and the current type is auth_capture, use potential id from
        // external_auth type if it exists. This is to set the credit card correctly when modifying an
        // order that used OnePayment, which changes auth_capture to external_auth.
        const paymentExtId =
          !paymentExtIdOfCurrentAuthType && vm.authType === PAYMENT_TYPES.AUTH_CAPTURE
            ? paymentExtIdOfExternalAuthType
            : undefined

        return fetchPaymentInstruments(paymentExtId).catch(() => {
          const { defaultErrorMessage, ok } = vm.lang.values
          errorModalService.open(defaultErrorMessage, ok)
        })
      }

      if (vm.requestUserPaymentOnFirstFetch) {
        Restangular.one('user')
          .post('payer', {
            payment_type: vm.authType,
            service_type: vm.paymentContext,
            auth_capture: {},
          })
          .then(firstFetch)
      } else {
        firstFetch()
      }
    },
    template,
  })
