import angular from 'angular'

// Keep constants alpha
const CART = 'cart'
const CART_PAYMENT = 'cart/payment'
const GEOCODE_URL = 'https://api.mapbox.com/geocoding/v5/mapbox.places'
const PROMO_CODES = 'promo_codes'
const TIME_SLOTS = 'timeslots'

export default angular
  .module('ApiServicesModule', [])
  .service('api', function api($http, Restangular, config) {
    const service = this

    Object.assign(service, {
      getCartPayment: params => Restangular.one(CART_PAYMENT).get(params),
      getCartPromoCodes: promo => Restangular.one(CART).customPOST(promo, PROMO_CODES),
      getTimeSlotsList: opts => Restangular.all(TIME_SLOTS).getList(opts),
    })

    service.getGeocodeAddress = address => {
      const getGeocodeURL = address => {
        return `${GEOCODE_URL}/${address}.json?country=us,ca&access_token=${config.mapboxglAccessToken}`
      }

      return $http.get(getGeocodeURL(address))
    }
  })
