import angular from 'angular'
import tokens from '@app/angular/services/modules/tokens'
import { concatProductTitle, getEnterpriseSaleEndDate } from '@app/utils/productHelpers'
import { product as ProductService } from '@instacart/enterprise-services'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { isWaysToShopEnabled } from '@app/utils/isModuleEnabled'

import { SHOPPING_SELECTOR_STAGES } from '@app/angular/constants/shoppingSelectorStages'
import OrderAddOnConfirmModalController from '../orderAddOn/orderAddOnConfirmModalController'
import OrderAddOnConfirmModalTemplate from '../orderAddOn/orderAddOnConfirmModal.html'

// The ext_data object displays data provided by the BE
// This array can be used to hide certain keys
const EXCLUDED_EXT_DATA = ['max_in_cart', 'availability_score', 'retailer_reference_code']

export default angular
  .module('ProductDetailsControllerModule', [OrderAddOnConfirmModalController.name])
  .controller(
    'ProductDetailsController',
    function (
      $filter,
      $rootScope,
      $scope,
      $state,
      $stateParams,
      $timeout,
      $uibModal,
      $window,
      Restangular,
      addressList,
      analytics,
      auth,
      cartAnalytics,
      cartService,
      config,
      eComLightService,
      facetedSearchService,
      lang,
      mobileService,
      offersService,
      oneCartService,
      placement,
      product,
      productId,
      registrationService,
      reservationService,
      shoppingSelectorService,
      tagsService,
      titleService,
      userService,
      utilService
    ) {
      const enableFacets = config.enableFacetedSearchFilters || config.enableICSearch
      $scope.config = config
      $scope.auth = auth
      $scope.lang = lang
      $scope.tagsService = tagsService
      $scope.facetedSearchService = facetedSearchService
      $scope.shoppingSelectorService = shoppingSelectorService
      $scope.reservationService = reservationService
      $scope.placement = placement
      $scope.product = null
      $scope.productOffer = null
      $scope.priceAlertId = null
      $scope.maxInCart = null
      $scope.currentCategory = $stateParams.category_id
      $scope.inEComLightMode = eComLightService.inEComLightMode
      $scope.inOrderAddOnMode = $state.current.name === 'orderAddOn'
      $scope.showEbtRibbon =
        config.ebt?.enabled &&
        oneCartService.enabled() &&
        auth.user.store?.ebt_enabled_in_state &&
        auth.user.status !== 'trial' &&
        product.usa_snap_eligible
      $scope.isWaysToShopEnabled = isWaysToShopEnabled()
      $scope.productAtStore = true
      $scope.totalConfigPrice = {
        price: 0,
      }
      $scope.analyticScope = cartAnalytics.createNewAnalyticScope()
      $scope.goToRoute = (...args) => {
        if (!placement.isExternalRoutesDisabled) {
          $state.go(...args)
        }
      }
      $scope.$on('$destroy', () => {
        cartAnalytics.destroyAnalyticScope($scope.analyticScope)
      })

      const picCfg = config.components.productImageCarousel
      $scope.productImageCarousel = {
        lang: {
          rootAriaLabel: lang.aria.productImageCarousel,
          screenReaderInstructions: lang.aria.productImageCarouselInstructions,
          thumbnailAriaLabel: lang.aria.goToSlide,
        },
        zoomEnabled: picCfg.zoomEnabled,
        images: [],
      }

      $scope.excludedExtData = EXCLUDED_EXT_DATA
      // adjust sale and loyalty end date for IC catalog items due to logic
      // between IC and Unata
      product.sale_end_date = getEnterpriseSaleEndDate(product.sale_end_date, config)
      product.loyalty_end_date = getEnterpriseSaleEndDate(product.loyalty_end_date, config)

      // React related props for Add to Cart
      $scope.addToCartStyles = {
        buttonGroup: mobileService.isMobile
          ? {
              width: 'calc(100% - 40px)',
              margin: '0 auto',
            }
          : {
              width: 230,
              margin: 0,
            },
      }

      $scope.hasExtData = false
      $scope.formattedDescription = ''
      $scope.showMoreOpen = false

      let descriptionTruncated = ''

      $scope.getFormattedPrepTime = function () {
        // Contains an array of lang keys and counts for lang.pluralize
        const components = []

        const hours = Math.floor($scope.product.product_config.lead_time / 60)
        const remainderMinutes = $scope.product.product_config.lead_time % 60
        const days = Math.floor(hours / 24)
        const remainderHours = hours % 24

        components.push({ key: 'day', count: days })
        components.push({ key: 'hour', count: remainderHours })
        components.push({ key: 'minute', count: remainderMinutes })

        return components
          .filter(c => c.count > 0)
          .map(c => `${c.count} ${lang.pluralize(c.key, c.count)}`)
          .join(' ')
      }

      // TODO: We need to refactor price alerts, this should not be necessary.
      $rootScope.$on('priceAlertChange', (event, { priceAlert }) => {
        $scope.product.price_alert = priceAlert
      })

      const barcodeScannerStateNames = ['main.scanBarcode', 'main.barcodeNotFound']

      // If we are accessing product details in a modal, there will not be a product details view state.
      $scope.isProductDetailsModal = $state.current.name !== 'main.product'

      // load facets to ensure they are available to get the values
      if (!$scope.isProductDetailsModal && enableFacets) {
        facetedSearchService.getAllFacets($stateParams)
      }

      $scope.getTagName = tag => {
        return enableFacets ? facetedSearchService.getTagName(tag) : tagsService.byVal(tag)?.name
      }

      $scope.showBackToScanner =
        mobileService.isMobileApp() &&
        barcodeScannerStateNames.indexOf($rootScope.previousState) !== -1

      $scope.loadProductDetails = () => {
        const setProduct = product => {
          $scope.product = product
          $scope.productAtStore = product.status === true
          $scope.product.placement = $scope.placement.promoted
          $scope.product.sponsoredBy = $scope.placement.source
          // This check also exists in the add to cart button,
          // but is required here to show the message
          $scope.maxInCart = product?.ext_data?.max_in_cart ?? null

          $scope.hasExtData = product?.ext_data ? Object.keys(product.ext_data)?.length > 0 : false

          // The hack to end all hacks. Emergency CUS Launch Requirement
          const SGW =
            'Government Warning: (1) According to the Surgeon General, women should not drink alcoholic beverages during pregnancy because of the risk of birth defects. (2) Consumption of alcoholic beverages impairs your ability to drive a car or operate machinery, and may cause health problems.'
          if (
            (product.warnings === null || product.warnings === undefined) &&
            product.is_alcoholic &&
            config.client === 'cus'
          ) {
            $scope.product.warnings = SGW
          }

          if ($scope.product.offers) {
            offersService.getOfferData($scope.product.offers[0].id).then(offerProgressData => {
              $scope.offer = offerProgressData
            })
          }
          const descriptionLimit = mobileService.isMobile
            ? config.productDetailsDescription.mobileLimit
            : config.productDetailsDescription.desktopLimit
          $scope.isDescriptionLong = product.description?.length >= descriptionLimit

          descriptionTruncated = $scope.isDescriptionLong
            ? `${$scope.product.description.slice(0, descriptionLimit)}...`
            : ''

          // If the read more button hasn't been clicked for a long description, show the truncated version
          $scope.formattedDescription =
            !$scope.showMoreOpen && $scope.isDescriptionLong
              ? descriptionTruncated
              : $scope.product.description

          if (product.offers) {
            $scope.productOffers = [...product.offers, ...cartService.getAllCartTypeOffers()]
          }

          if (!$scope.isProductDetailsModal) {
            $scope.title = titleService.setTitle(product.name)
          }

          if (!$rootScope.modifyingOrder) {
            analytics.logCriteoProductDetailsPage({
              item: product.ext_id,
              price: product.sale_price || product.base_price,
              availability: product.availability_status,
            })

            analytics.logSegmentEvent('store.view_product', {
              ...analytics.getProductInfoAnalytics($scope.product, null),
              ...analytics.getFeaturedProductImpression($scope.product.id),
              source: $scope.location,
            })

            analytics.logProductDetailsView(product)
          }

          if ($stateParams.tab) {
            $scope.tab = $stateParams.tab
            // Wait till next tick before scrolling
            $timeout($scope.scrollToTabs)
          } else if (!$scope.inEComLightMode && !$scope.inOrderAddOnMode) {
            $scope.tab = 'related'

            $scope.relatedLoading = true
            product
              .all('related')
              .getList({
                require_storeproduct: !config.enableUnavailableProductPDP,
                limit: 5,
              })
              .then(
                response => {
                  $scope.related = response.plain()
                  $scope.relatedLoading = false
                },
                () => {
                  $scope.related = []
                  $scope.relatedLoading = false
                }
              )
          } else if ($scope.product.ingredients) {
            $scope.tab = 'ingredients'
          } else if ($scope.product.instructions) {
            $scope.tab = 'instructions'
          } else if ($scope.product.nutrition) {
            $scope.tab = 'nutrition'
          }

          if ($scope.inOrderAddOnMode) {
            $scope.product.quantity = 1
          }

          $scope.productImageCarousel.images = [
            ...[$scope.product.images],
            ...$scope.product.additional_images,
          ]
            .slice(0, 6) // Only show a max of 6 thumbnails
            .map((image, i) => {
              return {
                small: image.tile.small,
                large: image.tile.large,
                altText:
                  $scope.product.additional_images.length > 0
                    ? `${$scope.product.name}, ${lang.values.view} ${i + 1}`
                    : $scope.product.name,
              }
            })

          $scope.productTags = $scope.tagsService.getTagsMatchingConfigTags(
            $scope.product.tags,
            'pdp'
          )
        }

        setProduct(product)
      }

      $scope.activateTab = function (tabValue) {
        const newTab = $scope.tab !== tabValue
        $scope.tab = tabValue
        // a11y functionality so when keyboard/screen reader user selects an already open tab focus will go to the
        // associated tab panel's content.
        if (!newTab) {
          document.getElementsByClassName('extras-content')[0]?.children[0]?.focus()
        }
      }

      // ENT-37048: do not display "Avg" size on PDP if configured to display PricingDisclosure
      $scope.productTitle = () =>
        concatProductTitle($scope.product, lang, !config.showPricingDisclaimerOnPdp)

      $scope.backToScanner = function () {
        $state.go(barcodeScannerStateNames[0])
      }

      // Order Add-On functionality
      $scope.orderAddOnIncrement = () => {
        if ($scope.product.quantity >= $scope.product.ordering_parameters.max) {
          $scope.product.quantity = $scope.product.ordering_parameters.max
        } else {
          // eslint-disable-next-line no-plusplus
          $scope.product.quantity++
        }
      }

      $scope.orderAddOnDecrement = () => {
        // eslint-disable-next-line no-plusplus
        if ($scope.product.quantity > 0) $scope.product.quantity--
      }

      $scope.orderAddOnUpdate = () => {
        if ($scope.product.quantity >= $scope.product.ordering_parameters.max) {
          $scope.product.quantity = $scope.product.ordering_parameters.max
        }
      }

      $scope.orderAddOnSubmit = product => {
        const modalOpen = function () {
          return $uibModal.open({
            template: OrderAddOnConfirmModalTemplate,
            controller: 'OrderAddOnConfirmModalController',
            resolve: {
              product,
            },
            backdrop: 'static',
            controllerAs: '$ctrl',
          }).result
        }

        modalOpen()
      }

      $scope.hasLoyaltyCard = () => userService.loyaltyCards.length > 0

      $scope.hasLoyaltyPrice = () => $scope.product.loyalty_price && config.enableLoyaltyPricing

      $scope.hasSalePrice = () => $scope.product.sale_price && !$scope.hasLoyaltyPrice()

      $scope.showOfferRibbon = () => {
        return (
          config.showOfferRibbonOnPdp &&
          $scope.product.offer_title &&
          $scope.product.offer_title.length > 0
        )
      }

      $scope.openTermsOfUse = () => {
        if (!config.userReviews.termsOfUseUrl) {
          return
        }

        $window.open(
          config.userReviews.termsOfUseUrl,
          mobileService.isMobileApp() ? '_system' : '_blank'
        )
      }

      $scope.openPDP = () => {
        $state.go('main.product', {
          id: $scope.product.id,
          name: $filter('slugify')($scope.product.name),
          placement: $scope.placement,
          tab: 'reviews',
        })
      }

      $scope.openLogin = () => {
        registrationService.open(
          'login',
          null,
          null,
          null,
          null,
          null,
          LOGIN_CONTEXTS.PRODUCT_USER_REVIEW
        )
      }

      $scope.scrollToTabs = () => {
        const el = document.querySelector('#product-details-extras')

        if (!el) {
          return
        }

        utilService.scrollIntoView(el, $scope.isModal && '.modal-content')
      }

      $scope.openAndScrollToTab = tab => {
        $scope.activateTab(tab)
        $scope.scrollToTabs()
      }

      $scope.showMoreDescription = () => {
        $scope.formattedDescription = !$scope.showMoreOpen
          ? $scope.product.description
          : descriptionTruncated
        $scope.showMoreOpen = !$scope.showMoreOpen
      }

      $scope.reviewsUserData = {
        id: auth.user.id,
        name: auth.getUserFullName(),
        location: addressList.getAddressRegionString(addressList.getDefault()),
        isTrial: auth.user.status === 'trial',
      }

      if (!$scope.product) {
        $scope.loadProductDetails()
      }

      $scope.priceStyles = {
        root: { fontSize: tokens.size.font.body },
        amount: { fontSize: tokens.size.font.h3 },
      }

      $scope.isProductAvailable = ProductService.utils.isProductAvailable($scope.product)
      $scope.shopContextIntent = auth.getContext().intent
      $scope.SHOPPING_MODES = SHOPPING_MODES

      $scope.showPricingPromos =
        !$scope.product.product_config ||
        $scope.product.product_config.config_type !== 'custom' ||
        $scope.product.product_config.has_pricing === false

      $scope.openStoreSelectorModal = () =>
        shoppingSelectorService.open(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR)
    }
  )
  .run($templateCache => {
    $templateCache.put(
      'productDetailsMainContent.html',
      require('./productDetailsMainContent.html')
    )
    $templateCache.put(
      'productDetailsDescription.html',
      require('./productDetailsDescription.html')
    )
    $templateCache.put('productDetailsPrice.html', require('./productDetailsPrice.html'))
    $templateCache.put('productDetailsTabs.html', require('./productDetailsTabs.html'))
  })
