import angular from 'angular'

export default angular
  .module('TimeslotSelectorStateControllerModule', [])
  .controller(
    'TimeslotSelectorStateController',
    function ($scope, $state, $stateParams, mobileService, redirectService, timeslotService) {
      $scope.redirect = $stateParams && $stateParams.redirect
      $scope.mobileService = mobileService

      $scope.redirectToPreviousState = () => {
        $scope.selectedTimeslot = true
        if ($state.current.name === 'main.timeslot') {
          redirectService.goToRedirectUrl(
            { href: $stateParams && $stateParams.redirect },
            { state: 'main.home' }
          )
        }
      }

      $scope.timeslotsAreSelectable =
        !timeslotService.areTimeslotsSelectableAtCheckoutOnly() ||
        $state.current.name === 'main.checkout.reservation.confirmation'
    }
  )
