import _ from 'lodash'
import angular from 'angular'
import dayjs from 'dayjs'

import template from './flyerPreviewModal.html'
import FlyerPreviewModalControllerModule from './flyerPreviewModal.controller'

export default angular
  .module('FlyerPreviewComponentModule', [FlyerPreviewModalControllerModule.name])
  .component('flyerPreview', {
    bindings: {
      flyerData: '<?',
      previewToken: '@?',
    },
    controller($uibModal, config, flyerPreviewService) {
      this.hasPreviewFlyers = flyerPreviewService.hasPreviewFlyers(this.flyerData)

      if (this.hasPreviewFlyers) {
        this.currentFlyer = this.flyerData.current
        this.previewFlyers = this.flyerData.previews

        // Add date range labels

        const addDateRangeLabel = flyer => {
          const startDate = dayjs(flyer.start_date)
          const endDate = dayjs(flyer.end_date).add(config.flyerPreviewOverlapDays, 'days')

          // Hide the start date year for the flyer being viewed unless they span over 2 years
          const startDateFormat = startDate.year() === endDate.year() ? 'MMM D' : 'MMM D, YYYY'

          flyer.dateRangeLabel = `${startDate.format(startDateFormat)} - ${endDate.format(
            'MMM D, YYYY'
          )}`
        }

        addDateRangeLabel(this.currentFlyer)
        _.each(this.previewFlyers, addDateRangeLabel)

        this.flyerBeingViewed = this.previewToken
          ? _.find(this.previewFlyers, { preview_token: this.previewToken })
          : this.currentFlyer

        this.openModal = () => {
          $uibModal.open({
            template,
            controller: 'FlyerPreviewModalController',
            controllerAs: '$ctrl',
            resolve: {
              currentFlyer: () => this.currentFlyer,
              previewFlyers: () => this.previewFlyers,
              previewToken: () => this.previewToken,
            },
          })
        }
      }
    },
    template: `
      <span ng-if=":: $ctrl.hasPreviewFlyers" class="flyer-preview-info icon-calendar">
        Deals Between
        <button ng-click="$ctrl.openModal()" class="flyer-preview-modal-trigger" id="flyer-preview-modal-trigger">
          {{:: $ctrl.flyerBeingViewed.dateRangeLabel}}
        </span>
      </span>
    `,
  })
