import angular from 'angular'
import template from './sideCart.html'
import SideCartControllerModule from './sideCartController.js'

export default angular
  .module('SideCartComponentModule', [SideCartControllerModule.name])
  .config($stateProvider => {
    $stateProvider.state('main.sideCart', {
      url: '/side-cart',
      views: {
        'content@': {
          template,
          controller: 'SideCartController',
        },
      },
    })
  })
  .run($templateCache => {
    $templateCache.put('sideCartListItem.html', require('./sideCartListItem.html'))
    $templateCache.put('sideCartListMultiContext.html', require('./sideCartListMultiContext.html'))
    $templateCache.put('sideCartList.html', require('./sideCartList.html'))
  })
