import angular from 'angular'
import { USER_ACTIONS } from '@app/angular/constants/events'
import urlParser from '@app/utils/urlParser'
import InstacartCustomerLinkModalControllerModule from './instacartCustomerLinkModalController'
import InstacartCustomerLinkServiceServiceModule from './instacartCustomerLinkService'

import ModalTemplate from './views/instacartCustomerLinkModal.html'

export default angular
  .module('InstacartCustomerLinkModalServiceModule', [
    InstacartCustomerLinkModalControllerModule.name,
    InstacartCustomerLinkServiceServiceModule.name,
  ])
  .service(
    'instacartCustomerLinkModalService',
    function instacartCustomerLinkModalService(
      $location,
      $q,
      $rootScope,
      $state,
      $uibModal,
      auth,
      config,
      errorModalService,
      instacartCustomerLinkService,
      lang,
      localStorageService
    ) {
      const service = this

      const skippable = () => {
        // if it's in checkout, it isn't skippable
        const urlParts = urlParser($location.absUrl())
        // check the pathname as well in case the application is loaded from url
        return !(urlParts.pathname.includes('checkout') || $state.includes('main.checkout'))
      }

      service.openModal = function ({
        startStage,
        linkLater = true,
        forceSkippableState = false,
      } = {}) {
        return $uibModal.open({
          template: ModalTemplate,
          controller: 'instacartCustomerLinkModalController',
          keyboard: true,
          backdrop: 'static',
          resolve: {
            startStage() {
              return startStage
            },
            skippable() {
              if (forceSkippableState) {
                return true
              }

              return skippable()
            },
            linkLater() {
              return linkLater
            },
          },
        }).result
      }

      service.shouldShowModal = () => {
        const deferred = $q.defer()
        // reject if the user is not logged in, not in delivery, or the feature is not enabled.
        if (
          auth.user.status === 'trial' ||
          (auth.getContext() && auth.getContext().intent !== 'delivery') ||
          !config.instacart.enableAccountLinking
        ) {
          deferred.reject()
        } else {
          let linked = auth.user.ic_linked

          if (!linked) {
            // if not linked, call the user/services endpoint to try to link again in case there was an issue the first time the link was attempted
            service
              .hasAccountBeenCreated()
              .then(() => {
                linked = true
              })
              .finally(() => {
                if (!skippable()) {
                  linked ? deferred.reject() : deferred.resolve()
                } else {
                  !localStorageService.deprecatedGet('ic_linklater') && !linked
                    ? deferred.resolve()
                    : deferred.reject()
                }
              })
          } else if (!skippable()) {
            linked ? deferred.reject() : deferred.resolve()
          } else {
            !localStorageService.deprecatedGet('ic_linklater') && !linked
              ? deferred.resolve()
              : deferred.reject()
          }
        }
        return deferred.promise
      }

      service.hasAccountBeenCreated = () => {
        // POST /user/services
        const deferred = $q.defer()
        instacartCustomerLinkService
          .retryLinking()
          .then(
            user => {
              auth.updateUser(user)
              // account linked, don't show modal
              user.ic_linked ? deferred.resolve() : deferred.reject()
            },
            response => {
              if (response && response.status && response.status === 400) {
                // invalid email. dont stop because of this.
                deferred.resolve()
              } else {
                errorModalService.open(
                  response.data ? response.data.message : response.message,
                  lang.values.ok
                )
                deferred.reject()
              }
            }
          )
          .catch(() => {
            deferred.reject()
          })
        return deferred.promise
      }

      service.handleCancelledModal = (previousState, previousParams) => {
        if (previousState.name) {
          $state.go(previousState.name, previousParams)
        } else {
          $state.go('main.home')
        }
      }

      service.handleNewLogin = (state, params) => {
        // Redirect trial users that login to the review cart page, as their cart may have changed.
        if ($state.includes('main.checkout')) {
          $state.go('main.checkout.cart')
        } else {
          // Successully logged in / registered, continue to requested state
          $state.go(state.name, params)
        }
      }
    }
  )
  .run(($rootScope, $state, auth, config, instacartCustomerLinkModalService) => {
    $rootScope.$on('$stateChangeStart', (event, state, params, previousState, previousParams) => {
      // show the modal if the user is logged in and goes to a route that requires instacart account linked
      if (
        auth.user &&
        auth.user.status !== 'trial' &&
        state.data &&
        state.data.requireICAccountLink &&
        auth.getContext().intent === 'delivery'
      ) {
        instacartCustomerLinkModalService.shouldShowModal(event).then(() => {
          event.preventDefault()
          const startStage = state.data.ic_link_start_stage || ''
          instacartCustomerLinkModalService
            .openModal(startStage)
            .then(() => {
              $state.go(state.name, params)
            })
            .catch(() => {
              instacartCustomerLinkModalService.handleCancelledModal(previousState, previousParams)
            })
            .finally(() => {
              $rootScope.isAppLoading = false
            })
        })
      }
    })

    $rootScope.$on('contextSelectionComplete', (_event, _action) => {
      instacartCustomerLinkModalService.shouldShowModal().then(() => {
        instacartCustomerLinkModalService.openModal()
      })
    })

    $rootScope.$on('auth', (event, action, state, params, previousState, previousParams) => {
      // after the user logged in through gated routes
      if (action === 'requiredLink' && state.data.requireICAccountLink) {
        instacartCustomerLinkModalService
          .shouldShowModal()
          .then(
            () => {
              const startStage = state.data.ic_link_start_stage || ''
              instacartCustomerLinkModalService
                .openModal(startStage)
                .then(() => {
                  instacartCustomerLinkModalService.handleNewLogin(state, params)
                })
                .catch(() => {
                  instacartCustomerLinkModalService.handleCancelledModal(
                    previousState,
                    previousParams
                  )
                })
                .finally(() => {
                  $rootScope.isAppLoading = false
                })
            },
            () => {
              instacartCustomerLinkModalService.handleNewLogin(state, params)
            }
          )
          .catch(() => {
            instacartCustomerLinkModalService.handleNewLogin(state, params)
          })
      }
      // this is to capture the gigya login.
      if (action === 'userLoggedIn' && config.gigya && config.gigya.enabled) {
        instacartCustomerLinkModalService.shouldShowModal().then(() => {
          instacartCustomerLinkModalService.openModal()
        })
      }
      // after user does a 'regular' log in
      if (action === USER_ACTIONS.POST_USER_LOGGED_IN_STATE_RELOAD) {
        instacartCustomerLinkModalService.shouldShowModal().then(() => {
          instacartCustomerLinkModalService.openModal()
        })
      }
    })
  })
