import angular from 'angular'

export default angular
  .module('RateProductsComponentModule', [])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.account.rateProducts', {
      url: '/rate-products',
      views: {
        'content@main.account': {
          template: require('./rateProducts.html'),
          controller: 'RateProductsController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "rateProductsController", webpackPrefetch: true */ './rateProductsController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.rateProducts)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.profile,
          },
          {
            label: langProvider.values.rateProducts,
          },
        ],
      },
    })
  })
