import React, { Fragment, memo } from 'react'
import PropTypes from 'prop-types'
import {
  createStyles,
  ButtonGroup,
  Button,
  Text,
  GridContainer,
  GridItem,
  LazyImage,
  utils,
} from '@instacart/cocktail'
import Header from '../Header'

const useStyles = createStyles(
  tokens => ({
    root: {
      textAlign: 'center',
      paddingLeft: tokens.size.spacing.medium,
      paddingRight: tokens.size.spacing.medium,
    },
    valueProposition: {
      fontSize: tokens.size.font.large,
      margin: utils.toPx`0px 0px ${tokens.size.spacing.medium}`,
      [utils.mediaQuery.maxWidth(tokens.breakpoint.small)]: {
        margin: utils.toPx`0px 0px ${tokens.size.spacing.xSmall}`,
      },
    },
    name: {
      margin: utils.toPx`${tokens.size.spacing.xxxSmall} 0px`,
    },
    desc: {
      fontSize: tokens.size.font.large,
      display: 'inline-block',
      maxWidth: '242px',
    },
    header: {
      padding: tokens.size.spacing.medium,
      marginBottom: tokens.size.spacing.xxLarge,
    },

    buttonStyles: {
      display: 'flex',
      flexWrap: 'row wrap',
      justifyContent: 'center',
      margin: utils.toPx`${tokens.size.spacing.medium} 0px`,
    },

    disclaimer: {
      display: 'inline-block',
      maxWidth: '676px',
      color: tokens.color.text.secondary,
      fontFamily: tokens.font.family.secondary,
      textAlign: 'left',
    },
  }),
  { displayName: 'SubscriptionCheckoutMarketing' }
)

const SubscriptionMarketing = props => {
  const styles = useStyles(props)
  const {
    i18n,
    onStepComplete,
    onBuySubscriptionClicked,
    onGiftSubscriptionClicked,
    marketing,
    isGiftable,
    isPurchasable,
    assetLoadingFunction,
  } = props

  return (
    <Fragment>
      <Header css={styles.header} i18n={i18n.header} />
      <div css={styles.root}>
        <GridContainer alignContent="center">
          {i18n.valuePropositions.map((valueProposition, index) => (
            <GridItem xSmall={12} small={4} key={valueProposition.name}>
              <div css={styles.valueProposition}>
                <GridContainer justify="center" align="center" alignContent="center" spacing="none">
                  <GridItem small={12} xSmall={4}>
                    <LazyImage
                      alt={valueProposition.imageAltText}
                      src={assetLoadingFunction(marketing.valuePropositions[index].icon)}
                      height="85px"
                      width="85px"
                    />
                  </GridItem>
                  <GridItem small={12} xSmall={8}>
                    <div>
                      <Text variant="body" color="brandPrimary" css={styles.name}>
                        {valueProposition.name}
                      </Text>
                      <Text variant="caption" color="textPrimary" css={styles.desc}>
                        {valueProposition.desc}
                      </Text>
                    </div>
                  </GridItem>
                </GridContainer>
              </div>
            </GridItem>
          ))}
        </GridContainer>

        <div css={styles.buttonStyles}>
          <ButtonGroup>
            {isPurchasable && (
              <Button
                onClick={() => {
                  onBuySubscriptionClicked && onBuySubscriptionClicked()
                  onStepComplete()
                }}
              >
                {i18n.buyingSubscriptionButtonLabel}
              </Button>
            )}
            {isGiftable && (
              <Button
                onClick={() => {
                  onGiftSubscriptionClicked && onGiftSubscriptionClicked()
                  onStepComplete({ path: 'gifting' })
                }}
                variant="outline"
              >
                {i18n.giftingSubscriptionButtonLabel}
              </Button>
            )}
          </ButtonGroup>
        </div>

        <Text
          css={styles.disclaimer}
          variant="caption"
          dangerouslySetInnerHTML={{ __html: i18n.disclaimer }}
        />
      </div>
    </Fragment>
  )
}

SubscriptionMarketing.defaultProps = {
  assetLoadingFunction: url => url,
}

SubscriptionMarketing.propTypes = {
  /** User pressed the next button */
  onStepComplete: PropTypes.func,
  /** User pressed the buy subscription button */
  onBuySubscriptionClicked: PropTypes.func,
  /** User pressed the gift subscription button */
  onGiftSubscriptionClicked: PropTypes.func,
  /** marketing object with icon images */
  marketing: PropTypes.shape({
    valuePropositions: PropTypes.arrayOf(
      PropTypes.shape({
        icon: PropTypes.string,
      })
    ),
  }),
  /** Custom language strings */
  i18n: PropTypes.shape({
    // eslint-disable-next-line react/forbid-prop-types
    valuePropositions: PropTypes.array,
    buyingSubscriptionButtonLabel: PropTypes.string,
    giftingSubscriptionButtonLabel: PropTypes.string,
    disclaimer: PropTypes.string,
    header: PropTypes.shape({
      default: PropTypes.shape({
        title: PropTypes.string,
        subtitle: PropTypes.string,
      }),
    }),
  }).isRequired,
  /** Whether subscriptions can be gifted */
  isGiftable: PropTypes.bool,
  /** Whether subscriptions can be purchased */
  isPurchasable: PropTypes.bool,
  /** function to load the asset images, this is needed for webpack to bundle the assets */
  /** TODO: this can be removed once we move the images to a cdn and they don't have to be bundled */
  assetLoadingFunction: PropTypes.func,
}

export default memo(SubscriptionMarketing)
