import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

const Checkout = ({ i18n, route, paths, steps, onStateChange, ...restProps }) => {
  const [currentParams, setCurrentParams] = useState({
    path: paths && paths[0],
  })

  const getNextStep = () => {
    const nextStepIndex = steps.findIndex(step => step.key === route) + 1
    return nextStepIndex < steps.length ? steps[nextStepIndex] : steps[0]
  }
  const onStepComplete = params => {
    setCurrentParams({
      ...currentParams,
      ...params,
    })
    onStateChange({ currentRoute: getNextStep().key, currentParams })
  }

  useEffect(() => {
    const stepIndex = steps.findIndex(step => step.key === route)
    // This checks that every step before the current step is valid
    if (
      steps
        .slice(0, stepIndex + 1)
        .every(step => step.isValid({ currentRoute: route, currentParams }))
    ) {
      onStateChange({ currentRoute: route, currentParams })
    } else {
      onStateChange({ currentRoute: steps[stepIndex - 1].key, currentParams })
    }
  }, [route, currentParams, onStateChange, steps])

  const renderedSteps = steps.map(Step => {
    return {
      ...Step,
      /* eslint-disable react/jsx-pascal-case */
      component: (
        <Step.component
          i18n={i18n[Step.key]}
          key={Step.key}
          onStepComplete={onStepComplete}
          {...currentParams}
          {...restProps}
        />
      ),
      /* eslint-enable react/jsx-pascal-case */
    }
  })

  return renderedSteps.find(step => step.key === route).component || renderedSteps[0].component
}

Checkout.defaultProps = {
  onStateChange: () => {},
}

Checkout.propTypes = {
  /** Steps for the checkout process */
  // eslint-disable-next-line react/forbid-prop-types
  steps: PropTypes.array,
  /** intial route to enter in the checkout process */
  route: PropTypes.string.isRequired,
  /** list of possible paths the checkout can take */
  paths: PropTypes.arrayOf(PropTypes.string),
  /** language object of objects with strings that correspond to each step */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Callback function when the state changes */
  onStateChange: PropTypes.func,
}

export default Checkout
