import angular from 'angular'

export default angular.module('bgImgFadeComponentModule', []).directive('bgImgFade', function () {
  return {
    restrict: 'A',
    link(scope, element, attrs) {
      scope.$watch(attrs.bgImgFade, value => {
        element.removeClass(attrs.addClassOnLoad)
        const url = value
        element[0].style.backgroundImage = `url(${url})`
        const img = new Image()
        img.onload = function () {
          element.addClass(attrs.addClassOnLoad)
        }
        img.src = url
      })
    },
  }
})
