import angular from 'angular'

export default angular
  .module('AddressAutocompleteModule', [])
  .component('addressAutocompleteInput', {
    bindings: {
      inputAutoFocus: '<',
      inputClass: '@',
      inputId: '@',
      inputName: '@',
      inputRequired: '<',
      inputPlaceholder: '<',
      inputChangedCallback: '<',
      addressChosenCallback: '<',
      hasError: '&',
      initialAddress: '<',
      showStreetAddress: '@',
      setAutoCompleteOptionSelected: '=',
    },
    controller(
      $element,
      $interval,
      $scope,
      $timeout,
      lang,
      addressSearchService,
      errorModalService,
      mobileService
    ) {
      this.$onInit = () => {
        // Service
        this.lang = lang

        // Internal States
        this.inputText = ''
        this.autoCorrectResults = 0
        this.autocomplete = null
        this.autocompleteListener = null
        this.pacContainer = null

        // Instance names
        this.autocompleteResultId = `${this.inputId}-autocomplete-results`
        this.instructionId = `${this.inputId}-instructions`
      }

      this.$postLink = () => {
        this.addressInputField = $element[0].children[0]

        if (this.inputAutoFocus) {
          this.addressInputField.focus()
        }

        // Set pre-filled address
        if (this.initialAddress) {
          this.inputText = this.initialAddress
        }

        // Initialize address autocomplete service with a11y enhancements
        const handleArrowKeys = event => {
          if (event.keyCode === 38 || event.keyCode === 40) {
            this.addressInputField.setAttribute('aria-autocomplete', 'both')
          }
        }

        const mutationCallback = mutationsList => {
          // eslint-disable-next-line no-restricted-syntax
          for (const mutation of mutationsList) {
            if (mutation.type === 'childList' && mutation.addedNodes.length === 1) {
              // Set a11y attributes for autocomplete results
              if (mobileService.isMobileApp()) {
                // For a11y on the mobile apps.
                // First we insert a label for the address value for screen readers
                const spanLabel = document.createElement('span')
                spanLabel.setAttribute('aria-label', mutation.addedNodes[0].innerText)
                spanLabel.setAttribute('class', 'screen-reader-text')
                mutation.addedNodes[0].prepend(spanLabel)
                // Next we use tabIndex attribute on the option container so that voiceover users can focus on an autocomplete suggestion
                mutation.addedNodes[0].setAttribute('tabIndex', '0')
              } else {
                // role="option" is required on the web for a11y but interferes with Talkback/Voiceover selecting an
                // autocomplete result in the Cordova app
                mutation.addedNodes[0].setAttribute('role', 'option')
              }
              const nodeID = mutation.addedNodes[0].innerText.replace(/\s/g, '')
              mutation.addedNodes[0].setAttribute('id', nodeID)
              // Set aria attributes on input field if there are autocomplete results
              this.addressInputField.setAttribute('aria-autocomplete', 'list')
              this.addressInputField.setAttribute('aria-expanded', true)
              this.addressInputField.setAttribute('aria-haspopup', 'listbox')
              this.addressInputField.setAttribute('aria-controls', this.autocompleteResultId)

              this.addressInputField.addEventListener('keydown', handleArrowKeys)

              $timeout(() => {
                // eslint-disable-next-line no-plusplus
                this.autoCorrectResults++
              })
            } else {
              // Targetting the 'mutation.addNotes.length === 0', ie. there are no autocomplete results,
              // remove/update the aria attributes
              this.addressInputField.setAttribute('aria-autocomplete', 'none')
              this.addressInputField.setAttribute('aria-expanded', false)
              this.addressInputField.removeAttribute('aria-haspopup')
              this.addressInputField.removeAttribute('aria-controls')
              this.autoCorrectResults = 0
            }
          }
        }

        const handleAddressSelected = address => {
          // Gets triggered when user press enter on input field or suggestion list
          // Also gets triggered when user tab out of the suggestion list while highlighting one of the addresses
          $scope.$apply(() => {
            if (this.showStreetAddress) {
              this.inputText = address.address1
            }

            if (this.addressChosenCallback) {
              address.address1 = address.address1 || this.inputText
              this.addressChosenCallback(address)
            }
          })
        }

        addressSearchService.sdkLoaded
          .then(() => {
            const [autocomplete, autocompleteListener] = addressSearchService.addAutoCompleteWidget(
              this.addressInputField,
              handleAddressSelected
            )

            // Attach autocomplete instance and listener to scope for clean up
            this.autocomplete = autocomplete
            this.autocompleteListener = autocompleteListener

            const interval = $interval(
              () => {
                if (this.pacContainer) {
                  return $interval.cancel(interval)
                }

                const autocompleteContainers = document.getElementsByClassName('pac-container')

                // Find autocomplete container associated with the input
                let containerIndex

                for (
                  containerIndex = 0;
                  containerIndex < autocompleteContainers.length;
                  containerIndex += 1
                ) {
                  if (!autocompleteContainers[containerIndex].id) {
                    this.pacContainer = autocompleteContainers[containerIndex]
                  }
                }

                // Set required a11y attributes on autocomplete container
                if (!mobileService.isMobileApp()) {
                  // listbox attribute prevents selection by Talkback/Voiceover users in the Cordova apps, so conditionally apply for web only
                  this.pacContainer?.setAttribute('role', 'listbox')
                }
                this.pacContainer?.setAttribute('aria-labelledby', 'continue-with-zip-code-label')
                this.pacContainer?.setAttribute('id', this.autocompleteResultId)

                // Move autocomplete results to be under the same parent as the input
                // By default this results node is appended to the body and automatically position themselves next to the input
                // Since we use autocomplete in modals, we need to move it to get the right stacking context
                this.addressInputField.parentNode.appendChild(this.pacContainer)

                // There's an issue where items in the autocomplete dropdown cannot
                // be selected on mobile devices. This fixes it.
                this.pacContainer.addEventListener('touchstart', evt => {
                  evt.stopImmediatePropagation()
                })

                const observer = new MutationObserver(mutationCallback)

                // We use this mutation observer to track when Google Maps SDK inserts a new
                // autocomplete suggestion in the list so we can then add a11y attributes to it (in mutationCallback)
                observer.observe(this.pacContainer, { childList: true })

                // Google map sdk overrides this attribute to off resulting in Chrome autofill
                // interfering with address autocomplete. Setting autocomplete attribute to
                // random string will disable Chrome autofill feature
                this.addressInputField.setAttribute(
                  'autocomplete',
                  `address-line-${Math.min(containerIndex, 2)}`
                )
              },
              300,
              10
            )
          })
          .catch(err => {
            errorModalService.open(err.data?.message || err.message, 'OK')
          })

        this.inputChanged = () => {
          if (this.inputText === '') {
            this.autoCorrectResults = 0
          }
          if (this.inputChangedCallback) {
            this.inputChangedCallback(this.inputText)
          }
        }

        // Track the autocomplete cursor for a11y
        this.trackAriaActiveDescendant = () => {
          $timeout(() => {
            const focusedAutocompleteElement = document.getElementsByClassName('pac-item-selected')
            if (focusedAutocompleteElement?.length > 0) {
              this.addressInputField.setAttribute(
                'aria-activedescendant',
                focusedAutocompleteElement[0].id
              )
            } else {
              this.clearActiveDescendant()
            }

            if (this.setAutoCompleteOptionSelected) {
              this.setAutoCompleteOptionSelected(!!focusedAutocompleteElement?.length)
            }
          })
        }

        this.clearActiveDescendant = () => {
          this.addressInputField.removeAttribute('aria-activedescendant')
        }

        this.onBlur = () => {
          this.clearActiveDescendant()

          if (this.addressChosenCallback) {
            this.addressChosenCallback({ address1: this.inputText })
          }
        }
      }

      this.$onDestroy = () => {
        addressSearchService.removeAutoCompleteWidget(
          this.autocomplete,
          this.autocompleteListener,
          this.pacContainer
        )
      }
    },
    template: `
      <input
        id="{{::$ctrl.inputId}}"
        class="{{::$ctrl.inputClass}}"
        type="text"
        name="{{::$ctrl.inputName}}"
        ng-model="$ctrl.inputText"
        ng-change="$ctrl.inputChanged(event)"
        ng-keyup="$ctrl.trackAriaActiveDescendant()"
        ng-class="{error: $ctrl.hasError()}"
        placeholder="{{::$ctrl.inputPlaceholder}}"
        aria-label="{{::$ctrl.lang.aria.enter + ' ' + $ctrl.lang.aria.address}}"
        aria-describedby="{{::$ctrl.instructionId}}"
        ng-blur="$ctrl.onBlur()"
        ng-required="::$ctrl.inputRequired"
      >
      <span class="validation-icon" aria-hidden="true"></span>
      <div id="{{::$ctrl.instructionId}}" class="screen-reader-text">{{$ctrl.lang.aria.addressAutocompleteInstructions}}</div>
      <div class="screen-reader-text" aria-live="assertive">{{$ctrl.lang.templateAsString( $ctrl.lang.aria.addressAutocompleteResultsMessage, { number:
        $ctrl.autoCorrectResults } )}}</div>
    `,
  })
