import angular from 'angular'
import { addErrorInterceptor } from '@app/utils/request'

const MAINTENANCE_MODE_HEADER = 'x-maintenance-mode'
const MAINTENANCE_VARIANTS = {
  maintenance: 'mantenance',
  siteDown: 'siteDown',
}

export default angular
  .module('MaintenanceComponentModule', [])

  // Route for maintenance view

  .config($stateProvider => {
    $stateProvider.state('maintenance', {
      url: '/maintenance',
      views: {
        'content@': {
          template: require('./maintenance.html'),
        },
      },
    })
  })

  // Intercept errors and redirect to maintenance view

  .run(($rootScope, $state, Restangular, maintenance) => {
    const showMaintainencePage = variant => {
      maintenance.setActive()
      maintenance.setVariant(variant)
      $rootScope.isAppLoading = false
      $state.go('maintenance', null, { location: false })
    }

    const handleError = (status, error, hasMaintenanceHeader) => {
      // Maintenance mode is active
      if (hasMaintenanceHeader) {
        return showMaintainencePage()
      }

      // User session has been throttled
      if (status === 503 && error === 50301) {
        return showMaintainencePage(MAINTENANCE_VARIANTS.siteDown)
      }
    }

    // Setup error handler in enterprise-services
    addErrorInterceptor(response => {
      response
        .json()
        .then(res => {
          handleError(res.status, res.error, response.headers.has(MAINTENANCE_MODE_HEADER))
        })
        .catch(() => {
          handleError(undefined, undefined, response.headers.has(MAINTENANCE_MODE_HEADER))
        })
    })

    // Setup legacy Angular error handler
    Restangular.setErrorInterceptor(response => {
      handleError(
        response.status,
        response.error,
        Boolean(response.headers()[MAINTENANCE_MODE_HEADER])
      )
    })
  })

  // Service to keep track of whether we're in maintenance mode or not

  .service('maintenance', function maintenance() {
    const service = this

    service.active = false
    service.variant = MAINTENANCE_VARIANTS.maintenance

    service.setActive = () => {
      service.active = true
    }

    service.setInactive = () => {
      service.active = false
    }

    service.setVariant = variant => {
      if (variant && MAINTENANCE_VARIANTS[variant]) {
        service.variant = variant
      } else {
        service.variant = MAINTENANCE_VARIANTS.maintenance
      }
    }
  })

  // Controller for the maintenance view
  .controller('MaintenanceController', function ($scope, maintenance) {
    $scope.reload = function () {
      window.location.reload()
    }

    $scope.variant = maintenance.variant
  })
