import angular from 'angular'

export default angular
  .module('AddressBookComponentModule', [])

  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.account.addressBook', {
      url: '/delivery-address',
      views: {
        'content@main.account': {
          template: require('./addressBook.html'),
          controller: 'AddressBookController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "addressBookController", webpackPrefetch: true */ './addressBookController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.addressBookTitle)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.addressBookTitle,
          },
        ],
        section: 'addressBook',
      },
    })
  })
