import angular from 'angular'

export default angular.module('RecipeIngredientsModule', []).component('recipeIngredients', {
  bindings: {
    ingredients: '<',
    ingredientServings: '<?', // only needed for mobile layout
  },
  controller(mobileService) {
    this.isMobile = mobileService.isMobile
  },
  template: `
      <section class="recipe-ingredients">

        <div ng-if=":: !$ctrl.isMobile">
          <h2 class="recipe-ingredients-title title-cell">Ingredients</h2>
        </div>

        <div ng-if="::($ctrl.isMobile && $ctrl.ingredientServings)" class="recipe-mobile-servings">
          <span class="recipe-mobile-servings-icon icon-servings" aria-hidden="true"></span>
          <span class="recipe-mobile-servings-text">Servings:
            <span>
              {{::$ctrl.ingredientServings}}
            </span>
          </span>
        </div>

        <ul>
          <li ng-repeat="ingredient in ::$ctrl.ingredients track by $index">
            {{::ingredient}}
          </li>
        </ul>

      </section>
    `,
})
