import angular from 'angular'
import template from './rating.html'

export default angular
  .module('RatingComponentModule', [])
  .directive('ratingDirective', function () {
    return {
      scope: {
        product: '=',
        onRate: '=',
      },
      template,
      controller($rootScope, $scope, Restangular, config, lang) {
        $scope.config = config
        $scope.lang = lang
        $scope.isDisabled = true
        $scope.reco_rating = Math.max(1, Math.round($scope.product.reco_rating))

        function ratingUpdatedCallback(res) {
          $scope.product.user_rating = res.rating
          if ($scope.onRate) {
            $scope.onRate(res)
          }
        }

        $scope.setRating = function (rating, product) {
          Restangular.one('storeproduct_ratings')
            .post(null, {
              product_id: product.id,
              rating,
            })
            .then(res => {
              $rootScope.$broadcast('productRatingChange', res, product.id)
            })
        }

        // Listen for change ot a product rating and update it so that if you have a product tile open while the product detail modal is open, updating
        // the rating will cause the UI to update in both contexts.

        $rootScope.$on('productRatingChange', (event, ratingItem, productId) => {
          if (productId === $scope.product.id) {
            ratingUpdatedCallback(ratingItem)
          }
        })

        $scope.$watch(['product'], (newVal, oldVal) => {
          if (newVal !== oldVal) {
            if ($scope.product) {
              $scope.rating =
                Math.round(
                  $scope.product.user_rating
                    ? $scope.product.user_rating
                    : $scope.product.reco_rating
                ) || 0
            } else {
              $scope.rating = 0
            }
          }
        })
      },
    }
  })
