import angular from 'angular'
import { isEmpty } from 'lodash'
import { CART_TYPES } from '@app/angular/constants/cart'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { IC_PLACEMENTS } from '@app/angular/constants/express'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { OneCartActions } from '@app/services/oneCart/types'
import { getStickyHeaderHeight } from '@app/utils/getStickyHeaderHeight'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import template from './sideCart.html'
import checkoutService from '../../services/modules/checkout.service'
import { auth as authService } from '@instacart/enterprise-services'
import { isRxProduct } from '@instacart/enterprise-services/product/utils'
import loadAssetImage from '@app/utils/loadAssetImage'
import cartMetrics from '@app/services/cart/metrics'
import { undoItemRemoveId } from '@app/angular/services/modules/undoRemove'

const DOCUMENT_BODY_SIDECART_OPEN = 'side-cart-is-open'

export default angular
  .module('SideCartDirectiveModule', [])
  .directive(
    'sideCart',
    function (
      $controller,
      $document,
      $rootScope,
      $timeout,
      $uibModalStack,
      cartService,
      checkoutService,
      config,
      configurableProductService,
      eComLightService,
      sideCartService,
      oneCartService
    ) {
      return {
        template,
        controller: function (
          $attrs,
          $element,
          $filter,
          $rootScope,
          $scope,
          $state,
          auth,
          cartService,
          checkoutService,
          config,
          eComLightService,
          express,
          lang,
          loyaltyService,
          oneCartService,
          registrationService,
          reactCartLoyaltyBalanceDefaultProps
        ) {
          $scope.inEComLightMode = eComLightService.inEComLightMode
          $scope.config = config
          $scope.sideCartDropdownOpen = false
          $scope.stickyOffset = getStickyHeaderHeight($scope.inEComLightMode)
          $scope.currencyFilter = $filter('currency')

          const setTotalRewards = () => {
            $scope.rewards = loyaltyService.getVisibleRewards(
              cartService.cart?.cart_totals?.[cartService.getCurrentCartType()]?.reward_discounts
            )
          }
          if (config.rewards.visible.length) {
            $scope.$watch(
              () =>
                cartService.cart?.cart_totals?.[cartService.getCurrentCartType()]?.reward_discounts,
              setTotalRewards
            )
            setTotalRewards()
          }

          $scope.auth = auth
          $scope.loyaltyReactProps = reactCartLoyaltyBalanceDefaultProps

          $scope.areAllMultiContextCartsEmpty = () => {
            // The first key should always have items as we delete carts without items
            const [firstKeyMultiCart] = Object.keys($scope.dataByCartType)
            return !$scope.dataByCartType[firstKeyMultiCart]?.items?.length
          }

          $scope.loadImg = image => loadAssetImage(`${image}`)

          $scope.inCatering = auth.getCurrentShoppingMode() === SHOPPING_MODES.CATERING

          $scope.isCartFullyEmpty = () =>
            $scope.useMultiContextCart
              ? $scope.areAllMultiContextCartsEmpty()
              : cartService.countCustomItems() === 0 && isEmpty($scope.categorizedProducts)

          $scope.handleCheckoutClick = event => {
            // fire a datadog action
            cartMetrics.sideCart.checkout()

            event.preventDefault()
            const { checkout, newCheckoutFlow, oneCart } = config

            if (oneCartService.enabled()) {
              if (auth.user.status === 'trial') {
                return registrationService.open(
                  'login',
                  null,
                  null,
                  null,
                  null,
                  null,
                  LOGIN_CONTEXTS.REDIRECT_ONECART_CHECKOUT
                )
              }
              return authService.api.auth
                .getOneCartCheckoutRedirect()
                .then(resp => {
                  oneCartService.redirectToPBI(resp.location, resp.auth_token, {
                    action: OneCartActions.CHECKOUT,
                  })
                })
                .catch(resp => {
                  if (resp && resp.error === HTTP_ERRORS.PII_UNAUTHORIZED) {
                    // If PII has timed out
                    registrationService.promptLogin(
                      $state.current.name,
                      $state.params,
                      '',
                      '',
                      lang.values.loginToContinue,
                      LOGIN_CONTEXTS.REDIRECT_ONECART_CHECKOUT
                    )
                  }
                })
            }

            const cartType =
              eComLightService.inEComLightMode || $state.params.type === CART_TYPES.CATERING
                ? CART_TYPES.CATERING
                : CART_TYPES.GROCERY

            if (
              !cartService.isCartBelowMin() &&
              checkout.sidecart.redirectToCheckout &&
              newCheckoutFlow.checkoutPage.enabled
            ) {
              $state.go('main.checkout.checkoutPage', { cart_type: cartType })
              return
            }

            $state.go('main.checkout.cart', { cart_type: cartType })
          }

          $controller('SideCartController', {
            $scope: $scope,
            $element: $element,
          })

          $scope.isRxProduct = isRxProduct

          // Adds a CSS class to the body element to prevent scrolling when the side cart is open
          // This prevents a double scrollbar which is very ugly
          const bodyEl = angular.element($document[0].body)
          $scope.$watch(
            () => $rootScope.isSideCartOpen,
            isSideCartOpen => {
              if (isSideCartOpen) {
                bodyEl.addClass(DOCUMENT_BODY_SIDECART_OPEN)
              } else {
                // Time should match css' .side-cart.slideInRight
                $timeout(() => bodyEl.removeClass(DOCUMENT_BODY_SIDECART_OPEN), 750)
              }
            }
          )

          $scope.useMultiContextCart = config.useMultiContextCart

          $scope.showIcExpressSideCart = () => {
            return express.shouldShowDynamicPlacement(IC_PLACEMENTS.SIDECART)
          }

          $scope.dismissIcExpressSideCart = () => {
            express.acknowledgePlacement(IC_PLACEMENTS.SIDECART)
          }

          $scope.acceptIcExpressSideCart = () => {
            sideCartService.closeSideCart()
            $scope.dismissIcExpressSideCart()
            $state.go('main.icExpress')
          }

          $scope.showLoyaltySideCartPlacement = () =>
            loyaltyService.showLoyaltyPlacement() && config.loyalty.enableLoyaltyOptIn.sideCart

          // NEW STUFF HERE
          $rootScope.$on('sideCart', (_, action) => {
            switch (action) {
              case 'open':
                sideCartService.openSideCart()
                break
              case 'close':
                sideCartService.closeSideCart()
                break
              default:
                console.warn(`The action ${action} is not supported by the sideCart`)
            }
          })

          // Opens the sidecart automatically
          if (
            $state.params.open_sidecart &&
            auth.getCurrentShoppingMode() !== SHOPPING_MODES.INSTORE_LIST
          ) {
            cartService.cartFetchPromise.then(() => {
              sideCartService.openSideCart()
            })
          }

          eventEmitter.on(EVENT_TYPES.NAV_SIDE_CART_OPEN, () => {
            sideCartService.goToNextStep()
          })
        },
        link: function (scope, element, attrs) {
          // Handle opening / closing the side cart on various devices
          // This is a bit of a cluster***k because we need to handle touch devices as well
          // and allowing tapping the cart to open, without triggering the links,
          // as well as touching outside to close it, without triggering THOSE links

          let headerTitleEl
          let contentEl

          const hover = event => {
            /**
             * When OneCart is enabled, we control when to add the 'hover' class to the sideCart element. The presence of the class
             * is what controls whether the side cart is visible or not. For OneCart, the side cart should open only when the users
             * click on the cart button in the header
             */
            const isHoverStateControlledManually = oneCartService.enabled()

            if (event && !element.hasClass('hover')) {
              event.preventDefault() // Make sure clicks on the side-cart content don't launch
            }
            $document.on('click', cancelHover) // Bind event to close when clicking outside side-cart
            if (!isHoverStateControlledManually) {
              sideCartService.openSideCart()
            }
            scope.sideCartDropdownOpen = true
            scope.$digest()
          }

          const attachHandlers = function () {
            if (
              !headerTitleEl &&
              (headerTitleEl = document.querySelector('.side-cart-header .title'))
            ) {
              // Toggle when clicking / touching header
              angular.element(headerTitleEl).on('click', headerClick)
            }

            if (!contentEl && (contentEl = document.querySelector('.side-cart-content'))) {
              // Open when clicking / touching content
              angular.element(contentEl).on('click', hover)
            }

            if (headerTitleEl && contentEl && observer) {
              observer.disconnect()
            }
          }

          if (MutationObserver) {
            var observer = new MutationObserver(attachHandlers)
            observer.observe(element[0].closest('[ui-view="sideCart"]'), {
              childList: true,
              subtree: true,
            })
          }

          // Call attachHandlers at least once in case MutationObserver is not present or DOM loads first
          attachHandlers()

          let clearCartModalVisible = false

          scope.configurableProductService = configurableProductService

          var cancelHover = function (event) {
            const undoRemoveToast = document.getElementById(undoItemRemoveId)
            if (undoRemoveToast && undoRemoveToast.contains(event.target)) return // Do not collapse when user is trying to re-add item that was removed by accident
            if (clearCartModalVisible) return // Do not collapse when the clear cart modal is shown.
            // Do not collapse when there is a modal shown (possibly a PDP modal)
            // Without this the cart hides when clicked inside the PDP modal.
            if (!!$uibModalStack.getTop()) return

            if (!(event.target instanceof HTMLSelectElement)) {
              // Make sure opening select lists within the side cart don't close it in IE
              element[0].querySelector('.side-cart-content').scrollTop = 0
              sideCartService.closeSideCart()
              $document.off('click', cancelHover)
              event.preventDefault() // Make sure clicks on links in the rest of the site don't launch
              event.stopPropagation()
            }
            scope.sideCartDropdownOpen = false
            scope.$digest()
          }

          element.on('click', event => {
            event.stopPropagation()
          })

          // Toggle on mouse enter / leave

          if (!oneCartService.enabled()) {
            element.on('mouseenter', hover)
            element.on('mouseleave', cancelHover)
          }

          var headerClick = function (event) {
            if (element.hasClass('hover')) {
              cancelHover(event)
            } else {
              hover(event)
            }
          }

          // The below two functions are used for keyboard-only controls where the user can tab into and out of the sidecart

          scope.tabEnter = function () {
            hover()
          }

          scope.tabExit = function () {
            if (!element[0].contains(document.activeElement)) {
              element[0].querySelector('.side-cart-content').scrollTop = 0
              sideCartService.closeSideCart()
              $document.off('click', cancelHover)
              scope.sideCartDropdownOpen = false
            }
          }

          scope.$on('cartService', (e, action, item) => {
            // The triggerClearModal, clearConfirm, and clearCancel actions are done to hide the side cart
            // dropdown menu because the ui bootstrap modal focuses on the empty cart button from this menu
            // which breaks the style of the side cart when the modal closes.

            switch (action) {
              case 'add':
                if (!oneCartService.enabled()) {
                  element.addClass('bump')
                  $timeout(() => element.removeClass('bump'), 200)
                }
                break
              case 'remove':
                if (oneCartService.enabled() && cartService.getItems().length === 0) {
                  sideCartService.closeSideCart()
                }
                break
              case 'triggerClearModal':
                clearCartModalVisible = true
                break
              case 'clearConfirm':
              case 'clearCancel':
                clearCartModalVisible = false
                if (oneCartService.enabled()) {
                  sideCartService.closeSideCart()
                }
                break
            }
          })
        },
      }
    }
  )
