import angular from 'angular'

export default angular
  .module('PromoCodeModalControllerModule', [])
  .controller(
    'PromoCodeModalController',
    function (
      $scope,
      $timeout,
      cartService,
      checkoutAnalytics,
      lang,
      mobileService,
      offerDetailsModalService,
      offersService,
      promoCodeOptions,
      promoCodeValidationService,
      refocusService
    ) {
      $scope.promoCodeOptions = promoCodeOptions
      $scope.error = null
      $scope.promoCodeValue = null

      if (promoCodeOptions.onClose) {
        $scope.$on('modal.closing', () => promoCodeOptions.onClose())
      }

      const setError = (errMsg, promoForm) => {
        $scope.error = errMsg
        promoForm.promoCode.$setValidity('promoCode', errMsg === null)
      }

      $scope.checkIfValidPromoCode = (promo, promoForm) => {
        // Reset error message on submit
        setError(null, promoForm)

        promoCodeValidationService
          .checkIfValidPromoCode(promo)
          .then(result => {
            const promoCode = promo.promo_code

            // If an offer has been unlocked the id will be returned to us
            if (result.offer_id) {
              offersService.getOfferData(result.offer_id).then(offer => {
                promoCodeOptions.onSuccess && promoCodeOptions.onSuccess()
                promoCodeOptions.setSuccessMessage &&
                  promoCodeOptions.setSuccessMessage(lang.values.promoCodeSuccessMessage)

                cartService.cart.promo_codes.push(promoCode)
                checkoutAnalytics.addPromo({ promoCode, offer, success: true })

                $scope.$dismiss()

                if (!$scope.promoCodeOptions.disableOfferModal) {
                  offerDetailsModalService.open(offer.offer)
                }
              })
            } else if (!result.offer_id && !$scope.promoCodeOptions.isUserLoggedIn) {
              setError(lang.values.promoCodeError, promoForm) // They may need to log in to unlock this offer
              if (mobileService.isMobileApp()) {
                // a11y fix for VoiceOver users to focus input that has errors
                document.getElementById('promo-code-input').focus()
              }
              checkoutAnalytics.addPromo({ promoCode, success: false })
            }
          })
          .catch(err => {
            setError(err.data?.message || err.message, promoForm)
            if (mobileService.isMobileApp()) {
              // a11y fix for VoiceOver users to focus input that has errors
              document.getElementById('promo-code-input').focus()
              // eslint-disable-next-line no-unused-expressions
            } else document.getElementById('promo-code-error-message')?.focus()
            checkoutAnalytics.addPromo({ promoCode: promo.promo_code, success: false })
          })
      }

      // Initialize `modal.closing` trigger to refocus invoking element when this modal closes
      refocusService.setRefocusTrigger($scope)
    }
  )
