import React, { useEffect } from 'react'
import PropTypes from 'prop-types'

import { config as configService, auth as authService } from '@instacart/enterprise-services'
import { useLoading, useSubscriptions } from './hooks'

import CartCheckoutSubscriptionSelectDummy from './CartCheckoutSubscriptionSelect'

const CartCheckoutSubscriptionSelectSmart = props => {
  // Subscribe to the services we need
  const config = configService.selectors.getConfig()
  const loadingService = useLoading()
  const subscriptionsService = useSubscriptions()

  // Destructure some objects for ease of use
  const { isLoaded } = loadingService.selectors
  const { GET_STORE_SUBSCRIPTIONS } = subscriptionsService.constants

  const subscriptions = subscriptionsService.selectors.getStoreSubscriptions()
  const userSubscription = authService.selectors.getUser()

  // Check if we need to fetch data
  useEffect(() => {
    if (!isLoaded(GET_STORE_SUBSCRIPTIONS)) {
      subscriptionsService.actions.getStoreSubscriptions()
    }
  }, [GET_STORE_SUBSCRIPTIONS, isLoaded, subscriptionsService.actions])

  // TODO (deprecated after 2.26): this conditional statement isn't needed as we won't be passing in subscriptions or cartitems anymore
  return props.subscriptions || props.cartItems ? (
    <CartCheckoutSubscriptionSelectDummy {...props} />
  ) : (
    <CartCheckoutSubscriptionSelectDummy
      {...props}
      subscriptions={subscriptions}
      config={config.subscriptions}
      userSubscription={userSubscription?.subscription}
      i18n={config.lang.subscriptions.cartCheckout}
    />
  )
}

// TODO (deprecated after 2.26): Not needed as we won't be using the cart to buy subscriptions anymore
CartCheckoutSubscriptionSelectSmart.propTypes = {
  /** Array of subscriptions to pick from */
  // eslint-disable-next-line react/forbid-prop-types
  subscriptions: PropTypes.array,
  /** Array of items currently in the cart */
  // eslint-disable-next-line react/forbid-prop-types
  cartItems: PropTypes.array,
}

export default CartCheckoutSubscriptionSelectSmart
