import dayjs from 'dayjs'

export const getProductOfferPoints = offers => {
  const offerWithPoints = offers.find(offer => offer.points !== null)
  return offerWithPoints ? offerWithPoints.points : null
}

export const getUnclippedCoupons = offers => {
  return offers.filter(offer => offer.offer_type === 'coupon' && !offer.clipped)
}

export const getProductCellBadgeClassName = config => {
  const {
    customBadge,
    hasOffer,
    hasOfferPoints,
    hasLoyaltyPoints,
    enableCustomOfferInsideBadge,
  } = config
  if (customBadge) {
    return `${customBadge.toLowerCase()}-badge`
  }
  if (hasOfferPoints && hasLoyaltyPoints) {
    return 'points-badge'
  }
  if (hasOffer && enableCustomOfferInsideBadge) {
    return 'custom-offer-inside-badge'
  }
  if (hasOffer) {
    return 'offer-badge'
  }
}

export const getProductPriceLabel = (product_config, lang) => {
  return product_config?.config_type === 'custom' && product_config?.has_pricing
    ? lang.values.from
    : lang.values.price
}

export const concatProductTitle = (product, lang, showAvg = true) => {
  if (product.average_weight && showAvg) {
    const averageWeight = lang.template(lang.values.avgWeight, {
      product: product.average_weight,
      unit: product.average_uom || product.display_uom,
    })
    return `${product.name} (${averageWeight})`
  }

  return product.name
}

export const getOverlayTriggerChance = (
  hasUnclippedCoupon,
  productCellCouponChance,
  productCellRecoChance
) => {
  const recoChance = hasUnclippedCoupon ? productCellCouponChance : productCellRecoChance
  if (recoChance > 0 && recoChance < 1) {
    return Math.random() <= recoChance
  }
  return recoChance
}

export const adjustDollarsToCents = amt => (amt >= 1 ? `$${amt}` : `${Math.floor(amt * 100)}`)

export const getConfigProductTags = (tagNames, configTags) => {
  let productTags = []
  if (tagNames) {
    // eslint-disable-next-line array-callback-return
    productTags = tagNames.map(tag => {
      // eslint-disable-next-line no-plusplus
      for (let i = 0; i < configTags.length; i++) {
        const configTag = configTags[i]
        if (configTag.id === tag) {
          return {
            background: configTag.color,
            icon: configTag.icon,
            name: configTag.name,
          }
        }
      }
    })
  }
  return productTags
}

// adjust start sale end date for IC catalog items due to logic
// between IC and Unata
export const getEnterpriseSaleEndDate = (saleEndDate, config) =>
  config.hasICSaleEndDate ? dayjs(saleEndDate).subtract(1, 'day') : saleEndDate
