import angular from 'angular'
import { EXPIRATION_DATE_FORMAT } from '@app/angular/constants/express'
import template from './firstStep.html'

class ExpressCancelFirstStep {
  constructor($scope, auth, config, express, lang) {
    Object.assign(this, {
      $scope,
      auth,
      config,
      express,
      lang,
      EXPIRATION_DATE_FORMAT,
      adList: [],
    })
  }

  $onInit() {
    this.express
      .getAdList()
      .then(list => (this.adList = list))
      .then(() => this.$scope.$apply())
  }

  getSubtitle() {
    const { trialSubtitle, membershipSubtitle } = this.config.icExpress.cancellation.firstStep

    return this.lang.template(this.express.isTrial() ? trialSubtitle : membershipSubtitle, {
      daysLeft: this.express.getMembershipRemainingDays(),
      expressName: this.config.icExpress.name,
    })
  }

  getRenewReminderText() {
    const { membership, trial } = this.lang.values.icExpress
    const plan = this.express.isTrial() ? trial : membership

    return this.lang.templateAsString(this.config.icExpress.cancellation.renewReminder, {
      plan,
    })
  }

  getBottomText() {
    const { trialBottomText, membershipBottomText } = this.config.icExpress.cancellation.firstStep
    const text = !this.express.isTrial() ? trialBottomText : membershipBottomText

    return this.lang.templateAsString(text, {
      plan: this.express.getPlanType(),
      expressName: this.config.icExpress.name,
    })
  }
}

export default angular
  .module('IcExpressCancelFirstStepComponentModule', [])
  .component('expressCancelFirstStep', {
    template,
    bindings: {
      useIcTheme: '<',
      onContinueClick: '&',
      onSendReminderClick: '&',
      termsOfService: '<',
    },
    controller: ExpressCancelFirstStep,
  })
