import angular from 'angular'

export default angular
  .module('productsServicesModule', [])
  .service(
    'productsService',
    function productsService($document, $filter, $state, Restangular, productDetailsModal) {
      const service = this

      service.getBeacon = beacon => {
        if (beacon) {
          return Restangular.one(beacon).get()
        }

        // Resolve rather than throwing an error since this is an optional request
        return new Promise(resolve => {
          resolve()
        })
      }

      service.cellOnClick = ({ location, locationValue, product, modal, placement = {} }) => {
        service.getBeacon(product.onClickBeacon)

        if (modal) {
          productDetailsModal.open(product.id, { location, locationValue }, placement)
        } else {
          $state.go('main.product', {
            id: product.id,
            placement,
            name: $filter('slugify')(product.name),
          })
        }
      }

      service.trackViewPort = ($element, $scope, product) => {
        if (!product.sponsored || !('IntersectionObserver' in window)) {
          return
        }

        if (!$scope.pxViewportObserver && !$scope.viewportObserver) {
          const sideCart = $document[0].querySelector('[side-cart]')
          const sideCartPeekWidth =
            sideCart && getComputedStyle(sideCart).getPropertyValue('--peek-width')
          const rightMargin = sideCartPeekWidth ? `-${sideCartPeekWidth.trim()}` : '0px'

          $scope.viewPortBeaconTimeouts = {}
          const getObserver = (getBeaconURL, options = {}, timeout = 0) =>
            new IntersectionObserver(
              (entries, observer) => {
                entries.forEach(({ isIntersecting, target }) => {
                  const url = getBeaconURL(target)

                  if (!isIntersecting) {
                    clearTimeout($scope.viewPortBeaconTimeouts[url])
                    return
                  }

                  $scope.viewPortBeaconTimeouts[url] = setTimeout(() => {
                    observer.unobserve(target)
                    service.getBeacon(url)
                  }, timeout)
                })
              },
              {
                rootMargin: `0px ${rightMargin} 0px 0px`,
                ...options,
              }
            )

          $scope.pxViewportObserver = getObserver(({ dataset }) => dataset.on1pxViewportBeacon)
          $scope.viewportObserver = getObserver(
            ({ dataset }) => dataset.onViewportBeacon,
            { threshold: 0.5 },
            1_000
          )
        }

        const element = $element[0]
        const { on1pxViewportBeacon, onViewportBeacon } = product
        if (on1pxViewportBeacon) {
          element.dataset.on1pxViewportBeacon = on1pxViewportBeacon
          $scope.pxViewportObserver.observe(element)
        }
        if (onViewportBeacon) {
          element.dataset.onViewportBeacon = onViewportBeacon
          $scope.viewportObserver.observe(element)
        }
      }
    }
  )
