import angular from 'angular'

export default angular.module('noticeModalComponentModule', []).component('noticeModal', {
  bindings: {
    browserWarning: '<',
  },
  transclude: true,
  controller(localStorageService) {
    const vm = this

    vm.dismissModal = () => {
      vm.modalIsDismissed = true
    }

    vm.dismissBrowserWarning = () => {
      localStorageService.deprecatedSet('ieBrowserWarningSeen', true)
      vm.dismissModal()
    }
  },
  template: `
      <div ng-if="!$ctrl.modalIsDismissed" class="notice-modal">
        <div class="message info">
          <button class="button smaller darker round icon-delete" ng-click="$ctrl.dismissModal()" ng-if="!$ctrl.browserWarning" aria-label="{{::lang.aria.modalClose}}"></button>
          <ng-transclude></ng-transclude>
          <span role="button" class="dismiss-text" ng-if="$ctrl.browserWarning" ng-click="$ctrl.dismissBrowserWarning()"><button>Dismiss</button></span>
        </div>
      </div>
    `,
})
