import dayjs from 'dayjs'
import angular from 'angular'
import { DATE_OF_BIRTH_FORMAT } from '@app/angular/constants/alcoholValidation'
import alcoholValidationModalTemplate from '../templates/alcoholValidation/alcoholValidationModal.html'

export default angular
  .module('AlcoholValidationServicesModule', [])
  .service('alcoholValidation', function ($state, $uibModal, auth, cartService, config, lang) {
    const service = this

    service.shouldShowModal = () => {
      const { dateOfBirth } = cartService

      return (
        cartService.getHasAlcohol() &&
        config.alcoholCompliance.enabled &&
        !service.isDateOfBirthValid(dateOfBirth)
      )
    }

    service.isDateOfBirthValid = date => {
      if (!date) return false

      return (
        dayjs(date, DATE_OF_BIRTH_FORMAT).format(DATE_OF_BIRTH_FORMAT) === date &&
        service.isBirthDateAlcoholEligible(date)
      )
    }

    service.isBirthDateAlcoholEligible = date => {
      return (
        dayjs().diff(dayjs(date, DATE_OF_BIRTH_FORMAT), 'years', true) >=
        config.alcoholCompliance.minAge
      )
    }

    service.getDateOfBirth = () => {
      return cartService.dateOfBirth
    }

    service.setDateOfBirth = date => {
      cartService.dateOfBirth = date
    }

    service.clearDateOfBirth = () => {
      delete cartService.dateOfBirth
    }

    service.open = (isDismissable = true) => {
      return $uibModal.open({
        template: alcoholValidationModalTemplate,
        controller($scope, $uibModalInstance) {
          Object.assign($scope, {
            lang,
            config,
            service,
            isDismissable,
            isDateOfBirthValid: false,
            dateOfBirth: cartService.dateOfBirth,
          })

          $scope.continue = () => {
            service.setDateOfBirth($scope.dateOfBirth)
            $uibModalInstance.close()
          }

          $scope.handleDismissClick = () => {
            if ($state.current.name === 'main.checkout.checkoutPage') {
              $state.go('main.checkout.cart')
            }
            $uibModalInstance.dismiss()
          }
        },
        keyboard: false,
        backdrop: 'static',
        controllerAs: '$ctrl',
      }).result
    }
  })
