import angular from 'angular'
import template from './cartCompareDialog.html'

class CartCompareDialog {
  constructor(lang, $filter) {
    Object.assign(this, {
      $filter,
      lang,
      cartLengthDiff: 0,
    })
  }

  $onChanges({ items }) {
    if (items && !items.isFirstChange() && items.previousValue !== items.currentValue) {
      this.cartLengthDiff = items.currentValue.length - items.previousValue.length
    }
  }

  onMessageDismiss() {
    this.cartLengthDiff = 0
  }

  /**
   * Sets a cart update message object based on the length diff of the cart
   * Originally from the cart-form component developed by Henrique
   */
  get updateMessage() {
    const { cartUpdateMessage, item, addedTo, removedFrom } = this.lang.values
    const changeSize = Math.abs(this.cartLengthDiff)
    const changeType = this.cartLengthDiff > 0 ? addedTo : removedFrom
    const itemText = this.$filter('pluralize')(item, changeSize)
    const body = this.lang.template(cartUpdateMessage, {
      changeSize,
      changeType,
      item: itemText,
    })

    return body
  }
}

export default angular.module('cartCompareDialog', []).component('cartCompareDialog', {
  bindings: {
    items: '<',
  },
  template,
  controller: CartCompareDialog,
})
