import angular from 'angular'
import {
  ANALYTICS,
  EVENTS,
  SOURCES,
} from '@app/angular/services/modules/analytics/analyticsConstants'

/*
 * To understand how checkout events work, please read the instructions here: _Analytics.service.md
 * The constants exist here: analyticsConstants.js
 */

export default angular
  .module('offerAnalyticsModule', [])
  .service(
    'offerAnalytics',
    function (analytics, analyticsHelper, auth, cartService, reservationService) {
      const service = this

      Object.assign(service, {
        auth,
        analytics,
        analyticsHelper,
        cartService,
        reservationService,
      })

      /**
       * Get default reused information as an object - that way no need to require dependencies all over the project
       * WARNING: !!! Only include easy to access information in here !!!
       *                 Ideally this should have a light footprint
       *
       * @return {object}
       */
      service.DEFAULTS = function () {
        const { analytics, auth, cartService, reservationService } = service

        return {
          [SOURCES.ANALYTICS_SERVICE]: analytics,
          [SOURCES.CART]: cartService.cart || null,
          [SOURCES.USER]: auth.user || null,
          [SOURCES.TIMESLOT]: reservationService.reservation?.timeslot || null,
        }
      }

      /**
       * Private: Helper method to get analytic payloads (default sources and "success" requirement both auto added)
       *
       * @param {object} source - Object of data to be used to generate analytic payloads
       * @param {array} requiredInfo - Array of constants defined above
       *
       * @returns {object|null}
       */
      service._createPayload = function (source = {}, requiredInfo = []) {
        return service.analyticsHelper.createPayload(
          { ...service.DEFAULTS(), ...source },
          requiredInfo
        )
      }

      // --- CART ANALYTIC METHODS --- //

      /**
       * Triggers analytics for viewing offers
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  analytics    - The analytics service
       *                             * {object}  cart         - The user cart object
       *                             * {object}  offers        - The current offers for the cart
       *                             * {object}  offerOrigin   - Information about the product through which the offer was viewed
       *                             * {object}  offerPageMeta - Information about the current page being viewed
       *                             * {boolean} is_modal      - Whether this offer is being viewed via a modal
       * @returns {undefined}
       */
      service.viewOffers = function (sources = {}) {
        sources.cart = service.cartService.cart

        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.OFFER.OFFER_DETAILS,
          ANALYTICS.OFFER.OFFER_ORIGIN,
        ])

        service.analyticsHelper.logAnalytics(EVENTS.STORE.VIEW_OFFERS, analyticsPayload, {
          offerPageMeta: sources.offerPageMeta,
          is_modal: !!sources.is_modal,
        })
      }

      /**
       * Triggers analytics for adding an offer
       *
       * @param {object} [sources] - An object with the following information (auto included: checkoutAnalytics.DEFAULTS):
       *                             * {object}  analytics    - The analytics service
       *                             * {object}  cart         - The user cart object
       *                             * {object}  offer         - The current offers for the cart
       * @returns {undefined}
       */
      service.addOffer = function (sources = {}) {
        sources.cart = service.cartService.cart

        const analyticsPayload = service._createPayload(sources, [
          ANALYTICS.CART.CART_ID,
          ANALYTICS.OFFER.OFFER_DETAILS,
        ])

        service.analyticsHelper.logAnalytics(EVENTS.STORE.ADD_OFFER, analyticsPayload)
      }
    }
  )
