import angular from 'angular'
import { product } from '@instacart/enterprise-services'
import { SHOPPING_MODES } from '@app/angular/constants/context'

import template from './templates/addToCartButton.ng.html'
import reactTemplate from './templates/addToCartButton.react.html'
import triggerTemplate from './templates/addToCartTrigger.html'
import modifyTemplate from './templates/addToCartModify.html'

export default angular
  .module('AddToCartModule', [])
  .directive('addToCart', function () {
    return {
      scope: {
        product: '=',
        small: '@',
        isDropdown: '=',
        location: '@',
        locationValue: '<',
        index: '<',
        configuration: '=?',
        existingCartItems: '=',
        isProductCell: '=',
        isRelatedProduct: '=?',
        existingConfigSingular: '=',
        isModal: '=?',
        reactStyles: '<',
        useReact: '<',
        color: '<',
        aggregateQuantity: '=',
        buttonType: '@',
        triggerCallback: '&',
        removeCallback: '&',
      },
      template: '<div ng-include="templateUrl"></div>',
      controller(
        $rootScope,
        $sce,
        $scope,
        $state,
        addToCartService,
        auth,
        analytics,
        cartAnalytics,
        cartService,
        collectionsService,
        config,
        configurableProductService,
        lang,
        mobileService,
        orderWeightOptionsService,
        productDetailsModal,
        productsService,
        rateApp,
        $element,
        capitalizeFilter
      ) {
        const element = $element[0]
        const isProductDetails = !$scope.isProductCell && !$scope.isRelatedProduct

        /*  React Template will be used under following conditions:
         *   - It's Product Details page
         *   - react.enableAddToCart is true in config
         *   - Product has no configurable products
         */

        $scope.isConfigurableProduct = $scope.product?.product_config?.options

        const ariaLabelAddText = capitalizeFilter(
          auth.getContext().intent === 'instore'
            ? lang.values.addToListButtonLabel
            : lang.values.addToCartButtonLabel
        )

        $scope.templateUrl =
          $scope.useReact && config.react.enableAddToCart && $scope.isConfigurableProduct
            ? 'templates/addToCartButton.react.html'
            : 'templates/addToCartButton.ng.html'

        // Overwrite the existing templateUrl if a type is provided
        if ($scope.buttonType === 'trigger') {
          $scope.templateUrl = 'templates/addToCartTrigger.html'
        }

        if ($scope.buttonType === 'modify') {
          $scope.templateUrl = 'templates/addToCartModify.html'
        }

        $scope.selectedQuantity = {
          value: null,
        }

        $scope.mobile = mobileService
        $scope.lang = lang
        $scope.auth = auth
        $scope.config = config
        $scope.cartAnalytics = cartAnalytics
        $scope.cartService = cartService
        $scope.analytics = analytics
        $scope.isProductDetails = isProductDetails
        $scope.productsService = productsService
        $scope.orderWeightOptionsService = orderWeightOptionsService
        $scope.idStringContext = $scope.location
          ? `-${$scope.location.toLowerCase().replace(/\s/g, '-')}`
          : ''
        $scope.addButtonARIALabel = `${ariaLabelAddText} ${$scope.product.name || ''}`
        $scope.isDynamicCollection = false

        // If a product max has been passed, check to see if the number of products in the cart exceeds the limit
        // This is used to disable and warn users
        // TODO - implement in react
        $scope.maxInCart = $scope.product?.ext_data?.max_in_cart ?? null
        $scope.hasReachedProductAddLimit = () =>
          $scope.maxInCart && ($scope.cartItem?.quantity || $scope.quantity) >= $scope.maxInCart

        // Check if the product is in stock
        $scope.isProductInStock = product.utils.isProductAvailable($scope.product)

        $scope.enableOutOfStockAdd = config.enableOutOfStockAddToCartContexts?.includes(
          auth.getContext().intent
        )

        if ($state.current.name === 'main.shop.collection' && $state.params.collection_id) {
          collectionsService
            .getCollection($state.params.collection_id, 1, false)
            .then(collection => {
              $scope.isDynamicCollection = collection.dynamic || false
            })
        }

        /*
         * HTML Popovers
         * This isn't an ideal way to do it, but I can't figure out how to make this DRY in Angular
         */
        $scope.maxQuantityWarningPopover = $sce.trustAsHtml(
          `<span class="icon-warning add-to-cart__warning" aria-hidden="true"></span> ${lang.template(
            lang.values.limitPerOrder,
            { limit: $scope.maxInCart }
          )}`
        )

        $scope.outOfStockWarningPopover = $sce.trustAsHtml(
          `<span class="icon-warning add-to-cart__warning" aria-hidden="true"></span> ${lang.values.outOfStockDescription}`
        )

        // Literally hacking this in for 2.23 for WEG launch, will create a PR to remove it in 2.24
        $scope.accessibleItemRemoved = () => {
          element.querySelector(`#add-to-cart-ada-decrement-${$scope.product.id}`).textContent = ''
          element.querySelector(`#add-to-cart-ada-decrement-${$scope.product.id}`).textContent = `${
            $scope.product.name
          } was removed from your shopping cart, you have ${
            $scope.quantity - 1
          } remaining in your cart`
        }
        // Ditto
        $scope.accessibleItemAdded = () => {
          element.querySelector(`#add-to-cart-ada-increment-${$scope.product.id}`).textContent = ''
          element.querySelector(`#add-to-cart-ada-increment-${$scope.product.id}`).textContent = `${
            $scope.product.name
          } was added to your shopping cart, you have ${$scope.quantity + 1} in your cart`
          setTimeout(() => {
            const addItemIncrementElement = element.querySelector(
              `#add-to-cart-button-cart-service-update-item-quantity-increment-${$scope.product.id}${$scope.idStringContext}`
            )
            if (addItemIncrementElement) {
              addItemIncrementElement.focus()
            }
          }, 0)
        }

        const context = auth.getContext().intent
        const isInstoreContent = context === 'instore'
        const addToCartConfigProps = config.reactProps.addToCart

        const { index } = $scope

        // Register analytics
        addToCartService.registerAddToCartAnalytics(element, index)

        // Functions
        const getCornerDistance = () => {
          return addToCartService.getCornerDistance(element, $scope.index)
        }

        const formatQuantity = (item = {}) => {
          return addToCartService.formatQuantity($scope.product, item.quantity)
        }

        const linkToProductDetails = productId => {
          productDetailsModal.open(productId, {
            location: $scope.location,
            locationValue: $scope.locationValue,
          })
        }

        // Add or update product by weight
        const updateWeight = (configuration, selectedQuantity) => {
          addToCartService.updateWeight({
            cartItem: $scope.cartItem,
            configuration: [configuration],
            element,
            index: $scope.index,
            isModal: $scope.isModal,
            location: $scope.location,
            locationValue: $scope.locationValue,
            product: $scope.product,
            quantity: $scope.quantity,
            selectedQuantity,
          })
        }

        const appReviewCheck = () => {
          addToCartService.appReviewCheck(rateApp)
        }

        // Tell the parent scopes when a product was added so the product cell can show recommended
        const tellParents = () => {
          addToCartService.tellParents($scope.product)
        }

        // REACT bindings
        $scope.passToCtrl = {}
        $scope.addToCartContent = ''
        $scope.variant = addToCartConfigProps.variant
        $scope.addIcon = $scope.product.order_by_weight
          ? addToCartConfigProps.addIcon.order_by_weight
          : addToCartConfigProps.addIcon.order_by_each
        // eslint-disable-next-line no-unused-expressions
        config.reactProps.addToCart.iconBefore
        $scope.labelButtonProps = {
          iconBefore:
            addToCartConfigProps.iconBefore[context] !== undefined
              ? addToCartConfigProps.iconBefore[context]
              : 'icon-cart',
          iconAfter: addToCartConfigProps.iconAfter[context],
        }
        $scope.placeholder = isInstoreContent
          ? lang.values.addToListButtonLabel
          : lang.values.addToCartButtonLabel

        $scope.items = []

        $scope.onAdd = () => {
          addToCartService.onAdd({
            element,
            index: $scope.index,
            isModal: $scope.isModal,
            location: $scope.location,
            locationValue: $scope.locationValue,
            passToCtrl: $scope.passToCtrl,
            product: $scope.product,
            quantity: $scope.quantity,
          })
        }

        $scope.onDropDownSelect = e => {
          updateWeight($scope.configuration, parseFloat(e.currentTarget.value))
          appReviewCheck()
        }

        $scope.onRemove = () => {
          addToCartService.onRemove({
            element,
            index: $scope.index,
            isModal: $scope.isModal,
            location: $scope.location,
            locationValue: $scope.locationValue,
            product: $scope.product,
            quantity: $scope.quantity,
          })
        }

        // Only implemented on trigger button
        $scope.onTrigger = () => {
          if ($scope.triggerCallback) {
            $scope.triggerCallback()
          }
        }

        // Only implemented on modify add to cart variant
        $scope.onAllItemsRemoved = () => {
          if ($scope.removeCallback && ($scope.quantity === 0 || $scope.quantity === 1)) {
            $scope.removeCallback()
          }
        }

        // Watch for changes to cart and update local scope
        function update(newConfiguration) {
          // Check if a new config is passed in via configurable product directive option click
          const updatedConfiguration = newConfiguration || $scope.configuration

          // Checks if there is no more than one config in cart (passed down from productCell)
          // This is to handle "select default" behaviour for configurable products
          if ($scope.product && !$scope.existingConfigSingular) {
            if (updatedConfiguration && $scope.product.product_config) {
              const selectedConfiguration = configurableProductService.formatConfigForCart(
                $scope.product,
                updatedConfiguration
              )

              // Checks if configuration already exists in cart
              const item = cartService.getItem($scope.product, selectedConfiguration)

              if (
                item &&
                item.product_config &&
                configurableProductService.isConfigEqual(
                  item.product_config.options,
                  selectedConfiguration
                )
              ) {
                $scope.cartItem = item
                $scope.quantity = item ? item.quantity : 0
                $scope.addToCartContent = formatQuantity(item)
              } else {
                $scope.cartItem = $scope.product
                $scope.quantity = item ? item.quantity : 0
                $scope.addToCartContent = formatQuantity($scope.product)
              }
            } else {
              // Checks if standard product already exists in cart
              const item = cartService.getItem($scope.product)
              $scope.cartItem = item
              $scope.quantity = item ? item.quantity : 0
              $scope.addToCartContent = formatQuantity(item)
            }
          } else if ($scope.existingConfigSingular) {
            $scope.cartItem = $scope.existingConfigSingular
            $scope.quantity = $scope.existingConfigSingular.quantity
          }

          $scope.selectedQuantity.value = $scope.quantity > 0 ? $scope.quantity : null

          const orderWeightOptions =
            $scope.product && orderWeightOptionsService.generate({ product: $scope.product })

          $scope.orderWeightOptions = orderWeightOptions
          $scope.items = mobileService.isMobile ? [] : orderWeightOptions
          $scope.addToCartContent = formatQuantity($scope.cartItem)
        }

        $scope.linkToProductDetails = linkToProductDetails
        $scope.updateWeight = updateWeight
        $scope.appReviewCheck = appReviewCheck
        $scope.tellParents = tellParents
        $scope.getCornerDistance = getCornerDistance

        // Deregister functions
        const deregisterGroupWatcher = $scope.$watchGroup(
          ['cartService.lastUpdated', 'product'],
          () => update()
        )
        const deregisterCartServiceWatcher = $scope.$on('cartService', (event, method, item) => {
          if (
            $scope.product &&
            (method === 'add' || method === 'remove') &&
            item &&
            item.store_product &&
            item.store_product.id === $scope.product.id
          ) {
            update()
          }
        })
        const deregisterConfigurableChangeWatcher = $scope.$on(
          'configurableProduct',
          (event, newConfiguration) => {
            update(newConfiguration)
          }
        )
        $scope.$on('$destroy', () => {
          deregisterGroupWatcher()
          deregisterCartServiceWatcher()
          deregisterConfigurableChangeWatcher()
        })

        $scope.getAddToCartLabel = () =>
          auth.getContext().intent === SHOPPING_MODES.INSTORE_LIST
            ? lang.values.addToListButtonLabel
            : $rootScope.modifyingOrder
            ? lang.values.addToOrderButtonLabel
            : lang.values.addToCartButtonLabel
      },
    }
  })
  .run($templateCache => {
    $templateCache.put('templates/addToCartModify.html', modifyTemplate)
    $templateCache.put('templates/addToCartTrigger.html', triggerTemplate)
    $templateCache.put('templates/addToCartButton.react.html', reactTemplate)
    $templateCache.put('templates/addToCartButton.ng.html', template)
  })
