import angular from 'angular'
import { EVENTS } from '@app/angular/services/modules/analytics/analyticsConstants'

export default angular
  .module('userAnalyticsModule', [])
  .service('userAnalytics', function UserAnalytics($rootScope, analytics, auth) {
    const service = this

    const logUserSegmentEvent = (event, metaData = {}) => {
      const [category, action] = event.split('.')
      // eslint-disable-next-line no-restricted-globals
      const userId = isNaN(auth.user?.ext_id) ? null : parseInt(auth.user?.ext_id, 10)

      // Log to GTM w. category, action, and userId
      analytics.logEvent(category, action, null, userId)

      // Log to Segment with the full event name & including extra metaData if provided
      analytics.logSegmentEvent(event, metaData)
    }

    service.logUserChangePassword = () => logUserSegmentEvent(EVENTS.USER.CHANGE_PASSWORD)
    service.logUserLinkLoyaltyCard = () => logUserSegmentEvent(EVENTS.USER.LINK_LOYALTY_CARD)
    service.logUserLogin = () => {
      logUserSegmentEvent(EVENTS.USER.LOGIN)
      analytics.logUserLogin()
    }
    service.logUserLoginAttempt = data => logUserSegmentEvent(EVENTS.USER.LOGIN_ATTEMPT, data)
    service.logUserLoginStart = data => logUserSegmentEvent(EVENTS.USER.LOGIN_START, data)
    service.logUserLogout = () => logUserSegmentEvent(EVENTS.USER.LOGOUT)
    service.logUserRegistrationAttempt = data =>
      logUserSegmentEvent(EVENTS.USER.REGISTRATION_ATTEMPT, data)
    service.logUserRegistrationExit = () => logUserSegmentEvent(EVENTS.USER.REGISTRATION_EXIT)
    service.logUserRegistrationStart = data =>
      logUserSegmentEvent(EVENTS.USER.REGISTRATION_START, data)
    service.logUserUpdateProfile = () => logUserSegmentEvent(EVENTS.USER.UPDATE_PROFILE)
    service.logUserLoyaltyOnboardingStart = () =>
      logUserSegmentEvent(EVENTS.USER.LOYALTY_ONBOARDING_START)
    service.logUserLoyaltyOnboardingExit = data =>
      logUserSegmentEvent(EVENTS.USER.LOYALTY_ONBOARDING_EXIT, data)
    service.logUserLoyaltyOnboardingForgotPassword = () =>
      logUserSegmentEvent(EVENTS.USER.LOYALTY_ONBOARDING_FORGOT_PASSWORD)
    service.logUserLoyaltyOnboardingSocialSignOn = data =>
      logUserSegmentEvent(EVENTS.USER.LOYALTY_ONBOARDING_SOCIAL_SIGN_ON, data)
    service.logUserLoyaltyOnboardingThankyouCTAClick = data =>
      logUserSegmentEvent(EVENTS.USER.LOYALTY_ONBOARDING_THANKYOU_CTA_CLICK, data)
    service.logUserLoyaltyOnboardingThankyouStart = () =>
      logUserSegmentEvent(EVENTS.USER.LOYALTY_ONBOARDING_THANKYOU_START)
    service.logUserAddEbtCard = () => logUserSegmentEvent(EVENTS.USER.EBT_ADD_CARD)
    service.logUserRemoveEbtCard = () => logUserSegmentEvent(EVENTS.USER.EBT_REMOVE_CARD)
    service.logUserEbtClientTokenizeFailed = data =>
      logUserSegmentEvent(EVENTS.USER.EBT_CLIENT_TOKENIZE_FAILED, data)
    service.logUserSSORedirect = () => logUserSegmentEvent(EVENTS.USER.SSO_REDIRECT)
    service.logUserForcedLogout = data => logUserSegmentEvent(EVENTS.USER.FORCED_LOGOUT, data)

    // Cannot add this to auth because of circular dependencies.
    $rootScope.$on('auth', (event, action) => {
      if (action === 'userLoggedIn') {
        service.logUserLogin()
      } else if (action === 'userLoggingOut') {
        service.logUserLogout()
      }
    })
  })
