import angular from 'angular'

export default angular
  .module('HouseAccountControllerModule', [])
  .controller(
    'HouseAccountController',
    function ($scope, config, houseAccountPayment, lang, reservationService) {
      $scope.config = config

      if (!$scope.payment.house_account) {
        $scope.payment.house_account = {}
      }

      $scope.$watch('payment.house_account.id', () => {
        houseAccountPayment.status.ready =
          !!$scope.payment.house_account && !!$scope.payment.house_account.id
      })

      const fulfillment_type = reservationService.reservation?.timeslot?.fulfillment_type
      const digitLength = config.maxHouseAccountLength

      $scope.inputPlaceHolder = config.shouldValidateHouseAccount
        ? lang.template(lang.values.houseAccountDigits, { digitLength })
        : lang.values.paymentTypes.house_account[fulfillment_type]

      $scope.inputPattern = config.houseAccountRegexPattern
        ? new RegExp(config.houseAccountRegexPattern)
        : ''
    }
  )
