import angular from 'angular'
import { getBannerConfig } from '@app/utils/banners'

export default angular.module('BannerTagsModule', []).directive('bannerTags', function () {
  return {
    scope: {
      bannerKey: '<',
      hasPickup: '<',
      hasDelivery: '<',
    },
    controller($scope, config, lang, mobileService) {
      $scope.bannerToDisplay = getBannerConfig($scope.bannerKey)
      $scope.mobile = mobileService
      $scope.lang = lang
    },
    link(scope, el) {
      const banner = scope.bannerToDisplay
      const { hasPickup, hasDelivery, lang } = scope

      if (!banner || (hasPickup === false && hasDelivery === false)) {
        return
      }

      let exclusiveContextText = ''
      if (hasPickup && !hasDelivery) exclusiveContextText = ` - ${lang.values.pickupOnly}`
      if (hasDelivery && !hasPickup) exclusiveContextText = ` - ${lang.values.deliveryOnly}`

      // Decorate the icons with properties that are defined
      if (banner.icon) {
        el.append(
          `<span>
            <span class="${banner.icon}" aria-hidden="true" />
          </span>`
        )
      } else {
        // hide the icon
        el.addClass('hide-empty')
      }

      if (banner.color) {
        el.css('background-color', banner.color)
      }

      if (banner.tagText) {
        if (!scope.mobile.isMobile) {
          el.append(`<span>${banner.tagText}<i>${exclusiveContextText}</i></span>`)
        }
      }
    },
  }
})
