/**
 * Overwrites the JSON-LD script in the head element
 *
 * @param {Object} scriptEl
 * @param {Object} structuredData
 */
const overwriteJsonLdInHead = (scriptEl, structuredData) => {
  const currentStructuredData = JSON.parse(scriptEl[0].innerHTML)
  if (currentStructuredData['@type']) {
    scriptEl[0].innerHTML = structuredData
  }
}

/**
 * Creates and adds a JSON-LD script in the head element
 *
 * @param {Object} structuredData
 */
const addJsonLdToHead = structuredData => {
  const scriptEl = document.createElement('script')

  scriptEl.setAttribute('type', 'application/ld+json')
  scriptEl.append(structuredData)

  document.getElementsByTagName('head')[0].appendChild(scriptEl)
}

/**
 * Gets the current script elements with type "application/ld+json"
 */
export const getCurrentJsonLd = () =>
  document.getElementsByTagName('head')[0].querySelectorAll('script[type="application/ld+json"]')

/**
 * Removes JSON-LD from head
 */
export const deleteJsonLd = () => {
  const el = document
    .getElementsByTagName('head')[0]
    .querySelectorAll('script[type="application/ld+json"]')[0]

  if (el) el.parentNode.removeChild(el)
}

/**
 * Checks the scripts to see if one already exists,
 * if so it overwrites, if not it creates on and sets it
 *
 * @param {Object} scriptEl
 * @param {Object} structuredData
 */
export const setJsonLdInHead = (scriptEl, structuredData) => {
  const stringifiedStructuredData = JSON.stringify(structuredData)

  if (scriptEl.length) {
    overwriteJsonLdInHead(scriptEl, stringifiedStructuredData)
  } else {
    addJsonLdToHead(stringifiedStructuredData)
  }
}
