import _ from 'lodash'
import angular from 'angular'
import urlParser from '@app/utils/urlParser'
import template from './videoModal.html'

export default angular
  .module('videoModalServiceComponentModule', [])

  // Service to provide access to launching the modal dialog

  .service('videoModalService', function videoModalService($uibModal) {
    const videoSites = [
      'youtube.com/watch',
      'youtube.com/embed/',
      'youtube.com/v/',
      'youtube.com/e/',
      'youtu.be',
      'vimeo.com',
    ]

    this.isVideoURL = function (url) {
      return _.some(videoSites, _.includes.bind(_, url))
    }

    function convertToEmbedURL(url) {
      const urlParts = urlParser(url)
      const query = new URLSearchParams(urlParts.search)
      const lastPathSegment = urlParts.pathname.slice(urlParts.pathname.lastIndexOf('/') + 1)

      switch (urlParts.hostname) {
        case 'www.youtube.com':
        case 'youtube.com':
        case 'youtu.be':
          return `https://www.youtube.com/embed/${query.get('v') || lastPathSegment}?autoplay=1`

        case 'vimeo.com':
          return `https://player.vimeo.com/video/${lastPathSegment}?autoplay=1`
      }
      return url
    }

    this.open = function (url) {
      const modal = $uibModal.open({
        template,
        controller: [
          '$scope',
          '$sce',
          function ($scope, $sce) {
            $scope.src = $sce.trustAsResourceUrl(convertToEmbedURL(url))
          },
        ],
        backdrop: 'static',
        windowClass: 'no-scroll',
      })

      return modal
    }
  })

  .run(($document, videoModalService) => {
    // Intercept all link clicks, and if the link is to a video hosting site, open video in is modal dialog instead.
    $document.on('click', e => {
      const target = e.target || e.srcElement
      if (target.tagName === 'A' && target.target !== '_blank') {
        const href = target.getAttribute('href')

        if (videoModalService.isVideoURL(href)) {
          e.preventDefault()
          videoModalService.open(href)
        }
      }
    })
  })
