import { BRAINTREE } from '@app/angular/constants/paymentGateway'
import angular from 'angular'
import promiseable from '@app/utils/promiseable'

export default angular
  .module('BraintreeIntegrationControllerModule', [])
  .controller(
    'BraintreeIntegrationController',
    function ($rootScope, $scope, $timeout, Restangular, braintreePayment, lang, utilService) {
      const onShowCardForm = $scope.onShowCardForm || $scope.$parent.onShowCardForm

      const getDependencies = promiseable(async () => {
        const hasBrainTree = typeof window.braintree !== 'undefined'
        if (!hasBrainTree) {
          await utilService.loadScript(BRAINTREE.JS_FILE)
        }
      })

      const setCreditCard = () => $scope.doSave && $scope.doSave()

      $scope.lang = lang
      $scope.hasCard = false
      $scope.brainTreeIsReady = false
      braintreePayment.status.ready = false

      $scope.getAuthToken = function () {
        return Restangular.one('cart')
          .one('payment')
          .customPUT({ payment_type: 'auth_capture' })
          .then(response => {
            $scope.auth_token = response.auth_capture.token
            $scope.hasCard = response.auth_capture.has_cards

            if ($scope.hasCard) {
              $timeout(setCreditCard)
            }
          })
      }

      const paymentMethodReceived = nonce => {
        $scope.$apply(() => {
          $scope.payment.auth_capture = {
            payment_instrument: {
              ext_id: nonce,
            },
          }

          // Remove Add Card button when nonce has been received
          if (!$scope.hasCard) {
            $timeout(setCreditCard)
            $scope.hasCard = true
          }

          braintreePayment.verifiedPayment()
        })
      }

      getDependencies()
        .then($scope.getAuthToken)
        .then(() => {
          window.braintree.setup($scope.auth_token, 'dropin', {
            container: 'iframeBraintree',

            onPaymentMethodReceived(payment_nonce) {
              paymentMethodReceived(payment_nonce.nonce)
            },

            paymentMethodNonceReceived(_, payment_nonce) {
              paymentMethodReceived(payment_nonce)
            },

            onReady() {
              $scope.brainTreeIsReady = true
              braintreePayment.status.ready = !!$scope.hasCard

              if (onShowCardForm) {
                onShowCardForm()
              }
            },
          })
        })

      $scope.$watchGroup(['hasCard', 'brainTreeIsReady'], ([hasCard, brainTreeIsReady]) => {
        braintreePayment.status.ready = !!hasCard && brainTreeIsReady

        if ($scope.toggleIsShowingAddCard) {
          $scope.toggleIsShowingAddCard(!hasCard)
        }
      })
    }
  )
