import angular from 'angular'

export default angular
  .module('InvalidFocusComponentModule', [])
  .directive('invalidFocus', function () {
    return {
      restrict: 'A',
      link: (scope, el) => {
        scope.directiveScope = scope.invalidFocusFn || {}
        scope.directiveScope.goToFocus = elementSelector => {
          const focusElement = el[0].querySelector(elementSelector)
          if (focusElement) {
            focusElement.focus()
          }
        }
      },
    }
  })
