import angular from 'angular'
import { ADDRESS_FIELDS } from '@app/angular/constants/address'
import { SHOPPING_SELECTOR_STAGES } from '@app/angular/constants/shoppingSelectorStages'
import unsupportedPostalCodeErrorModalTemplate from '../views/unsupportedPostalCodeErrorModalTemplate.html'
import { isUnsupportedPostalCode } from './helpers'

export default angular
  .module('StoreSelectorListControllerComponentModule', [])
  .controller(
    'StoreSelectorListController',
    function (
      $rootScope,
      $scope,
      addressList,
      analytics,
      auth,
      config,
      errorModalService,
      lang,
      storesService
    ) {
      $scope.auth = auth
      $scope.addressList = addressList
      $scope.stores = null
      $scope.loading = false
      $scope.config = config
      $scope.showInput = true
      $scope.ADDRESS_FIELDS = ADDRESS_FIELDS
      $scope.delivery = { postalCode: '' }
      $scope.isUnsupportedPostalCode = false
      $scope.autoCompleteOptionSelected = false

      $scope.requireFullAddress = config.shoppingContextProcess.delivery.requireFullAddress
      $scope.showMultipleDeliveryAddresses =
        config.enableMultipleDeliveryAddresses && auth.user.status !== 'trial'

      $scope.addressInputLabel = `${lang.values.enter} ${
        $scope.requireFullAddress ? lang.values.deliveryAddress : lang.values.postalCode
      }`

      $scope.addressInputChangedCallback = () => {
        $scope.$parent.clearShoppingSelectorStageErrorMessage(
          SHOPPING_SELECTOR_STAGES.STORE_SELECTOR
        )
        if ($scope.requireFullAddress) {
          $scope.delivery.postalCode = ''
        }
      }

      if ($scope.requireFullAddress) {
        $scope.addressChosenCallback = address => {
          $scope.selectedAddress = address
          $scope.delivery.postalCode = address.postal_code
          if ($scope.deliveryDefaultPrevented) {
            $scope.deliveryDefaultPrevented = false
            $scope.fetchStores()
          }
        }
      }

      $scope.addressListChosenCallback = address => {
        $scope.selectedAddress = address
        $scope.delivery.postalCode = address.postal_code
        return $scope.fetchStores()
      }

      $scope.fetchStores = function (e) {
        $scope.stores = null
        $scope.loading = true
        $scope.$parent.clearShoppingSelectorStageErrorMessage(
          SHOPPING_SELECTOR_STAGES.STORE_SELECTOR
        )

        if ($scope.$parent.shopContextIntent === 'delivery') {
          // Picking a store also results in submitting the form.
          if (e && e.type === 'submit') {
            if ($scope.requireFullAddress) {
              if (!$scope.delivery.postalCode) {
                e.preventDefault()
                $scope.deliveryDefaultPrevented = true
                // Do not show message when submit was triggered by hitting enter while user had
                // selected address from autocomplete list.
                // Hitting enter in this case will make autocomplete widget asynchronously  populate
                // address and trigger fetchStores
                if (!$scope.autoCompleteOptionSelected) {
                  $scope.$parent.setShoppingSelectorErrorMessage(
                    SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
                    lang.template(lang.values.deliveryFullAddressRequired)
                  )
                }
                $scope.loading = false
                return
              }
            } else {
              $scope.selectedAddress = {
                postal_code: $scope.delivery.postalCode?.toUpperCase().replace(/\s/g, ''),
              }
            }
          }

          // Display error message if no postal code is entered.

          if (!$scope.delivery.postalCode) {
            const errorTemplate = $scope.requireFullAddress
              ? lang.values.deliveryFullAddressRequired
              : lang.values.deliveryPostalCodeRequired
            $scope.$parent.setShoppingSelectorErrorMessage(
              SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
              lang.template(errorTemplate, { postalCode: lang.values.postalCode })
            )
            $scope.loading = false
            return false
          }

          const address = { ...$scope.selectedAddress }

          $scope.isUnsupportedPostalCode = isUnsupportedPostalCode(
            $scope.selectedAddress.postal_code
          )

          const openUnsupportedPostalCodeErrorModal = () => {
            if (config.enableUnsupportedPostalCodeCheck && $scope.isUnsupportedPostalCode) {
              $scope.$dismiss()
              errorModalService.openCustom(unsupportedPostalCodeErrorModalTemplate, { lang })
            }
          }

          const handleDeliveryUnavailable = () => {
            $scope.showInput = false
            $scope.delivery.postalCode = null
            const { postal_code } = address
            $scope.$parent.setShoppingSelectorErrorMessage(
              SHOPPING_SELECTOR_STAGES.STORE_SELECTOR,
              config.enableUnsupportedPostalCodeCheck && $scope.isUnsupportedPostalCode
                ? lang.values.unsupportedPostalCodeErrorMsg
                : lang.template(lang.values.wereSorryPostalCode, {
                    searchedPostalCode: lang.sanitize(postal_code),
                  })
            )

            $scope.loading = false
            if (config.enableUnsupportedPostalCodeCheck && $scope.isUnsupportedPostalCode) {
              openUnsupportedPostalCodeErrorModal()
            }
          }

          const fetchStores = params => {
            const paramsFromAddress = auth.getUrlParamsFromAddress(address)

            return storesService
              .fetch({
                ...params,
                ...paramsFromAddress,
                show_delivery: true,
              })
              .then(stores => {
                $scope.stores = stores
                if (stores.length > 0) {
                  let newStore = stores[0]

                  // Associated store will be selected if there is a match with retailer_slug
                  // of current context. Example: A user is on Spirits store type when switching
                  // store location AND new location has Spirits as well, THEN Spirits is selected
                  const currentShopContext = auth.getContext()
                  if (newStore.associated_stores.length) {
                    newStore.associated_stores.forEach(store => {
                      if (store.store_banner.key === currentShopContext.retailer_slug) {
                        newStore = store
                      }
                    })
                  }
                  // setting flag to indicate that store was changed for $rootScope.reloadState
                  $rootScope.deliveryStoreChangeIsInProgress = true

                  return $scope.$parent.updateDeliveryStore(newStore, address).then(() => {
                    $scope.loading = false
                  })
                } else {
                  // No stores available.
                  handleDeliveryUnavailable()
                }
              })
              .catch(err => {
                handleDeliveryUnavailable()
              })
          }

          const saveEnteredAddressToBook = () => {
            // Save new address to address book when.
            // 1. Address was entered to input and not selected from address book, in this case address does not have ID
            // 2. It is registered user
            // 3. Config requires to enter full address (not only ZipCode)
            if (!address.id && auth.user.status !== 'trial' && $scope.requireFullAddress) {
              auth.getProfile().then(({ first_name, last_name }) => {
                // for some reasons saveAddress() expects a short code of province/state in province field
                const { address1, city, province_short: province, postal_code } = address
                addressList
                  .saveAddress({
                    address1,
                    city,
                    province,
                    postal_code,
                    first_name,
                    last_name,
                  })
                  .then(address => {
                    addressList.markAddressValidated(address.id)
                    addressList.fetchAddresses()
                  })
              })
            }
          }

          const fetchBannersThenStores = () => {
            let deliveryAvailable = false

            return storesService
              .fetchBanners(auth.getUrlParamsFromAddress(address))
              .then(storeBanners => {
                if (storeBanners.is_ic_retailer === false) {
                  deliveryAvailable = true
                  return fetchStores()
                } else if (storeBanners.items.length > 0) {
                  deliveryAvailable = true
                  if (
                    storeBanners.items.length > 1 &&
                    !config.multipleBanners.enableNonTabulatedView
                  ) {
                    $scope.$parent.setDeliveryBannersAndAddress(storeBanners.items, address)
                    $scope.$parent.$parent.setShoppingSelectorStage(
                      SHOPPING_SELECTOR_STAGES.BANNER_SELECTOR
                    )
                    $scope.loading = false
                  } else {
                    return fetchStores({ retailer_slug: storeBanners.items[0] })
                  }
                } else {
                  // No banners available.
                  handleDeliveryUnavailable()
                }
              })
              .finally(args => {
                if (deliveryAvailable) {
                  saveEnteredAddressToBook()
                }
              })
          }
          return fetchBannersThenStores()

          // Delivery person is just assigned the store that they are given from the backend, based on ther postal / zip code
        }

        // We pass in the zip / postal code when we are looking for store for pickup person

        if ($scope.$parent.shopContextIntent === 'pickup') {
          storesService
            .fetch({
              show_pickup: true,
            })
            .then(stores => {
              $scope.stores = stores
              $scope.loading = false
            })
        }
        // We pass in the zip / postal code when we are looking for store for instore person

        if ($scope.$parent.shopContextIntent === 'instore') {
          storesService.fetch({}).then(stores => {
            $scope.stores = stores
            $scope.loading = false
          })
        }
      }

      if ($scope.$parent.shopContextIntent !== 'delivery') {
        $scope.fetchStores()
      }

      $scope.setAutoCompleteOptionSelected = val => ($scope.autoCompleteOptionSelected = val)
    }
  )
