import angular from 'angular'

export default angular
  .module('CartListTransferStateControllerComponentModule', [])
  .controller(
    'CartListTransferStateController',
    function (
      $scope,
      $stateParams,
      auth,
      cartListTransferService,
      cartService,
      compareCartService,
      lang
    ) {
      $scope.lang = lang
      $scope.cartListTransferLoaded = false

      const { initialContext } = $stateParams
      const { updatedContext } = $stateParams

      cartService.fetch().then(() => {
        compareCartService
          .compareCart(true, 'fulfillment', { fulfillment_type: updatedContext })
          .then(result => {
            cartListTransferService.initializeTransfer({
              initialContext,
              updatedContext,
              transferData: result,
            })
          })
          .then(() => auth.setContext({ intent: updatedContext }))
          .then(() => {
            $scope.cartListTransferLoaded = true
          })
      })
    }
  )
