import angular from 'angular'
import { MAX_TIP } from '../../../constants/checkout'

export default angular.module('TipPickerComponentModule', []).component('tipPicker', {
  bindings: {
    tipIsValidCallback: '&',
    onTipApplied: '&',
  },
  controller($element, analytics, cartService, checkoutAnalytics, config, lang, utilService) {
    const vm = this
    vm.lang = lang
    vm.config = config
    vm.cartService = cartService
    vm.maxTipValue = MAX_TIP
    vm.error = null
    vm.isFocused = false

    const invalidTipErrorMsg = lang.values.tipErrorMsg

    const setTipPercent = val => {
      cartService.setTip({ percentage: val })
    }

    const isValidTip = tipAmount => {
      return (
        tipAmount === '' || tipAmount === null || (tipAmount >= 0 && tipAmount <= vm.maxTipValue)
      )
    }

    const validateTip = () => {
      if (vm.currentTipPercent !== null || isValidTip(vm.currentTipValue)) {
        vm.error = null
        vm.tipIsValidCallback && vm.tipIsValidCallback({ isValid: true })
        return true
      }
      vm.error = invalidTipErrorMsg
      vm.tipIsValidCallback && vm.tipIsValidCallback({ isValid: false })
      return false
    }

    /**
     * Updates and syncs the cart when a tip percentage is selected.
     */
    vm.tipChangedPercent = val => {
      if (val === -1) {
        utilService.focusSelector('.tip-number-value', $element[0])
        vm.currentTipValue = Number(cartService.cart.tip_total)
      } else {
        setTipPercent(val)
      }

      if (val === 0) {
        cartService.cart.tip_total = val
        cartService.cart.tip_percent = val
      }

      const payload = {
        tip: {
          option: val,
          customAmount: null,
        },
      }

      cartService
        .sync()
        .then(() => checkoutAnalytics.editTipOptions({ ...payload, success: true }))
        .then(() => vm.onTipApplied())
        .catch(() => checkoutAnalytics.editTipOptions({ ...payload, success: false }))
    }

    /**
     * Updates and syncs the cart when a custom tip is entered.
     */
    vm.tipChangedValue = () => {
      if (!validateTip() || vm.currentTipValue === null) {
        vm.currentTipValue = 0
      }
      cartService.cart.tip_total = vm.currentTipValue
      cartService.cart.tip_percent = null

      const payload = {
        tip: {
          option: 'value',
          customAmount: vm.currentTipValue,
        },
      }

      cartService
        .sync()
        .then(() => checkoutAnalytics.editTipOptions({ ...payload, success: true }))
        .then(() => vm.onTipApplied())
        .catch(() => checkoutAnalytics.editTipOptions({ ...payload, success: false }))
    }

    const updateScopeValues = () => {
      vm.currentTipPercent =
        cartService.cart && cartService.cart.tip_percent !== null
          ? // eslint-disable-next-line radix
            parseInt(cartService.cart.tip_percent)
          : null
      vm.currentTipValue =
        cartService.cart && cartService.cart.tip_total !== null
          ? parseFloat(cartService.cart.tip_total)
          : null

      // User last entered a 'custom' tip.
      if (vm.currentTipPercent === -1) {
        vm.currentTipPercent = null
      }

      // User has not selected or entered any tip yet.
      if (
        !vm.isFocused &&
        cartService.cart &&
        vm.currentTipPercent === null &&
        vm.currentTipValue === null
      ) {
        if (config.defaultStaticTip) {
          vm.currentTipValue = parseFloat(config.defaultStaticTip)
        } else {
          vm.currentTipPercent = config.tipDefault
        }
        vm.tipChangedValue()
      }
    }
    vm.$onChanges = updateScopeValues
  },
  template: `
      <div class="tip-container" role="radiogroup" aria-label="{{::$ctrl.lang.values.tip}}">
        <div class="tip-container" ng-repeat="tip in $ctrl.config.tipPercents" ng-class="{'tip-percent-value': tip !== null, 'tip-custom-value': tip === null}">
          <input data-test="tip-percentage-input" name="tip-percent-value" id="tip-picker-{{::tip}}" type="radio" ng-model="$ctrl.currentTipPercent" ng-change="$ctrl.tipChangedPercent(tip)" ng-value="{{::tip}}" class="radio">
          <label for="tip-picker-{{::tip}}">{{::tip}}%</label>
        </div>
        <div class="tip-container">
          <input data-test="tip-amount-input" class="radio" name="tip-percent-value" id="tip-picker-null" type="radio" ng-model="$ctrl.currentTipPercent" ng-change="$ctrl.tipChangedPercent(-1)" ng-value="null">
          <label class="" for="tip-picker-null">{{::$ctrl.lang.values.customAmount}}</label>
        </div>
      </div>
      <div class="tip-container tip-custom-value">
        <input
          data-test="tip-amount-text-input"
          class="input small tip-number-value"
          type="number"
          min="0"
          max="{{$ctrl.maxTipValue}}"
          step="any"
          ng-model="$ctrl.currentTipValue"
          ng-show="$ctrl.currentTipPercent === null"
          ng-value="$ctrl.currentTipValue"
          ng-change="$ctrl.tipChangedValue()"
          ng-model-options="{ updateOn: 'default blur', debounce: { 'default': 1000, 'blur': 0 } }"
          aria-label="{{::$ctrl.lang.values.tip + $ctrl.lang.values.customAmount}}"
        >
      </div>
      <div class="tip-error" ng-if="$ctrl.error"><span class="icon-info"></span>{{$ctrl.error}}</div>
    `,
})
