import { config as configService, auth as authService } from '@instacart/enterprise-services'
import { windowManager } from '@instacart/enterprise-services-cordova'
import isMobileApp from '@app/utils/isMobileApp'
import { SSO_PROVIDERS } from '@app/angular/constants/ssoProviders'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'

export default function handleExternalAuth(provider: SSO_PROVIDERS) {
  return authService.promiseActions
    .getExternalAuthUrl({ identifier_polytype: provider })
    .then(resp => {
      const config = configService.selectors.getConfig()

      // This event is listened to by the AngularJS Redirect service
      eventEmitter.emit(EVENT_TYPES.SET_REGISTRATION_URL)

      // SSO providers that disallow SSO in the InAppBrowser
      const { disallowedInAppSSOProviders } = config.socialSignOn

      if (isMobileApp() && disallowedInAppSSOProviders.indexOf(provider) === -1) {
        return windowManager.openAuthUrl(resp.identifier_url, {
          closeOnHome: true,
          shouldResolve: true,
        })
      }
      window.open(resp.identifier_url, isMobileApp() ? '_system' : '_self')
      return new Promise(() => {})
    })
}
