import _ from 'lodash'
import angular from 'angular'
import dayjs from 'dayjs'

export default angular
  .module('flyerPreviewServicesModule', [])
  .service('flyerPreviewService', function flyerPreviewService($q, Restangular, config) {
    const emptyFlyerData = {
      current: null,
      previews: [],
    }

    this.flyerData = {} // Store flyer data against store id keys

    this.fetchFlyerData = storeId => {
      this.currentStoreId = storeId || 'noId' // If for whatever reason no store id is provided, store it against this.flyerData.noId

      if (this.flyerData[this.currentStoreId]) {
        return $q.resolve(this.flyerData[this.currentStoreId])
      }
      return Restangular.one('flyer/preview')
        .get()
        .then(flyerData => {
          this.flyerData[this.currentStoreId] = {
            current: flyerData.current || emptyFlyerData.current,
            previews: !_.isNull(flyerData.preview) ? [flyerData.preview] : emptyFlyerData.previews,
          }

          // If config'd to do so, show user the first available flyer preview (if it exists) every time we fetchFlyerData
          if (config.showPreviewFlyerAsDefault) {
            this.setFirstAvailablePreviewToken()
          }

          return this.flyerData[this.currentStoreId]
        })
        .catch(
          () =>
            // Set and return this.flyerData
            (this.flyerData[this.currentStoreId] = emptyFlyerData)
        )
    }

    // If a preview timestamp was passed in on app init, flyerData will not have been fetched, so return empty flyer data
    this.getFlyerData = () =>
      this.previewTimestamp ? emptyFlyerData : this.flyerData[this.currentStoreId]

    this.hasPreviewFlyers = flyerData =>
      flyerData && flyerData.previews && flyerData.previews.length > 0

    this.getPreviewTimestampDate = () =>
      this.previewTimestamp ? dayjs(this.previewTimestamp).format('D MMMM, YYYY') : ''

    this.getPreviewTimestamp = () => this.previewTimestamp

    this.setPreviewTimestamp = previewTimestamp => {
      this.previewTimestamp = previewTimestamp
    }

    this.getPreviewToken = () => this.previewToken

    this.setPreviewToken = previewToken => {
      this.previewToken = previewToken
    }

    this.setFirstAvailablePreviewToken = () => {
      if (this.hasPreviewFlyers(this.flyerData[this.currentStoreId])) {
        this.previewToken = this.flyerData[this.currentStoreId].previews[0].preview_token
      } else {
        // Clear preview token if the current store does not have a preview flyer
        this.previewToken = null
      }
    }

    this.getPreviewFlyerIndexBeingViewed = () => {
      if (this.previewToken) {
        return _.findIndex(this.flyerData[this.currentStoreId].previews, {
          preview_token: this.previewToken,
        })
      }
      return -1
    }
  })
