import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import template from './cartListTransfer.component.html'

export default angular.module('CartListTransferComponentModule', []).component('cartListTransfer', {
  template,
  controller(
    $state,
    auth,
    cartService,
    cartListTransferService,
    config,
    lang,
    mobileService,
    oneCartService,
    shoppingContextService
  ) {
    // We assume the cartListTransferService.initializeTransfer() has been called
    const {
      cancelContextSwitch,
      completeContextSwitch,
      transferData: { numCustom, numTotal, numTransferable },
      transferStatus,
    } = cartListTransferService

    const initialContext = auth.getContext().intent
    const updatedContext = shoppingContextService.selectedFulfillmentType

    Object.assign(this, {
      lang,
      config,
      transferStatus,
      numTransferable,
      cancelContextSwitch,
      completeContextSwitch,
      mobile: mobileService,
      cartListTransferService,
      hideButton: { yes: false },
    })

    this.transferItems = () => {
      this.hideButton.yes = true
      // We need to pass the a redirect route if we are in the cart review page and one cart is enabled
      cartListTransferService.transferItems(
        oneCartService.enabled() && $state.current.name === 'main.checkout.cartV2'
          ? 'main.home'
          : null
      )
    }

    this.loadImg = image => {
      // eslint-disable-next-line import/no-dynamic-require
      return require(`static/images/${image}`)
    }

    // Check if a redirect back to the .Com is necessary

    // eslint-disable-next-line eqeqeq
    this.shouldRedirectToDotCom = $state.current.name == 'cartListTransfer'

    // Determine copy to show user

    const initialCartOrList =
      initialContext === SHOPPING_MODES.INSTORE_LIST
        ? lang.values.inStoreList
        : lang.values.shoppingCart
    const updatedCartOrList =
      updatedContext === SHOPPING_MODES.INSTORE_LIST
        ? lang.values.inStoreList
        : lang.values.shoppingCart
    const initialContextTitle = shoppingContextService.getShoppingModeTitle(initialContext)
    const updatedContextTitle =
      config.shoppingSelector[updatedContext].shortTitle ||
      config.shoppingSelector[updatedContext].title

    this.stayInShoppingModeText = lang.template(lang.values.stayInShoppingMode, {
      shoppingMode: initialContextTitle,
    })

    if (numTransferable === 0) {
      // If NO products can be transfered
      this.cartListTransferTitle = lang.template(
        lang.values.transferBetweenCartAndList.titleTransferUnavailable,
        { initialContextTitle }
      )

      const unavailableText = lang.values.transferBetweenCartAndList.messageTransferUnavailable
      this.cartListTransferMsg = lang.template(unavailableText.msg, { updatedContextTitle })
      this.cartListTransferFollowUp =
        numCustom === 0
          ? lang.template(unavailableText.remainingProducts, { numTotal, initialCartOrList })
          : lang.template(unavailableText.remainingProductsWithCustom, {
              numTotal,
              initialCartOrList,
              numCustom,
            })
      this.doNotTransferTxt = lang.template(lang.values.transferBetweenCartAndList.yesContinue, {
        updatedContextTitle,
      })
    } else {
      // If products can be transfered
      this.cartListTransferTitle = lang.template(
        lang.values.transferBetweenCartAndList.titleTransferAvailable,
        { numTransferable, initialCartOrList }
      )

      const availableText = lang.values.transferBetweenCartAndList.messageTransferAvailable
      this.cartListTransferFollowUp = lang.template(availableText.question, { updatedCartOrList })
      this.doNotTransferTxt = lang.values.no

      if (numTransferable < numTotal) {
        // If SOME products can be transfered
        this.cartListTransferMsg = lang.template(availableText.someMsg, {
          numTransferable,
          numTotal,
          initialCartOrList,
          updatedContextTitle,
        })

        // Add note about the remaining products in their cart/list (with info about custom items if they have any)
        this.cartListTransferFollowUp += ` ${
          numCustom === 0
            ? lang.template(availableText.remainingProducts, { initialCartOrList })
            : lang.template(availableText.remainingProductsWithCustom, {
                numCustom,
                initialCartOrList,
              })
        }`
      } else {
        // If ALL products can be transfered
        this.cartListTransferMsg = lang.template(availableText.allMsg, {
          numTotal,
          initialCartOrList,
          updatedContextTitle,
        })
      }
    }
  },
})
