import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('ListPrintServicesModule', [])
  .service(
    'listPrintService',
    function listPrintService(
      $compile,
      $rootScope,
      $templateCache,
      $timeout,
      auth,
      configurableProductService
    ) {
      const service = this

      // Print a given list / cart
      service.printList = function (list, title) {
        // Categorize products, making sure no subscriptions or other products
        // without categories end up in the list

        const items = _.filter(list, item => {
          if (item.store_product && item.store_product.product_config) {
            // Create a configuration string if product has configurations
            item.configString = configurableProductService.configsString(item)
          }
          return (
            item.store_product &&
            item.store_product.categories &&
            item.store_product.categories.length > 0
          )
        })

        const categorizedItems = _.groupBy(items, item => {
          return item.store_product.categories[0]
            ? item.store_product.categories[0].name
            : 'Uncategorized'
        })

        const customItems = _.filter(list, { item_type: 'pseudo_product' })

        if (customItems.length) {
          categorizedItems['Custom Items'] = customItems
        }

        const templateScope = $rootScope.$new()
        templateScope.categorizedItems = categorizedItems
        templateScope.title = title
        // Add the store details.
        templateScope.store = auth.user.store

        const template = $templateCache.get('printInStoreList.html')
        const templateEl = $compile(template)(templateScope)[0]

        const printWindow = window.open()
        printWindow.addEventListener('beforeunload', () => {
          templateScope.$destroy()
        })

        // TODO: This is a very hacky way to get the proper css in the print template
        // a better solution would be to completely isolate out the css for this template
        const inlineStyles = [
          ...Array.prototype.slice.call(angular.element(document.querySelectorAll('head > style'))),
        ]
          .map(style => `${style.outerHTML}`)
          .join(' ')
        const linkedStyles = [
          ...Array.prototype.slice.call(
            angular.element(document.querySelectorAll('head > [rel="stylesheet"]'))
          ),
        ]
          .map(style => `<link href="${style.href}" rel="stylesheet">`)
          .join(' ')

        // Wait for digest to allow $compile to do its thing with templateEl
        $timeout(() => {
          printWindow.document.write(`
          <html>
            <head>
              <title>${title}</title>
              ${inlineStyles}
              ${linkedStyles}
            </head>
            <body onload="setTimeout(function() { window.print();setTimeout(function() { window.close(); }, 0)}, 250);">${templateEl.outerHTML}</body>
          </html>
        `)
          printWindow.document.close()
          printWindow.focus()
        })
      }
    }
  )
  .run($templateCache => {
    $templateCache.put(
      'printInStoreList.html',
      require('../../components/shared/util/printInStoreList.html')
    )
  })
