export default tag => {
  if (!tag) return

  let styles = {}
  const iconClass = `tag-icon icon-${tag.icon}`

  // because the default value for circle is assumed to be true, we need to check if the
  // config value has been explicitly set to false
  if (tag.circle === false) {
    styles = { icon: { color: `${tag.color} !important` } }
  } else {
    styles = { icon: { backgroundColor: `${tag.color} !important` } }
  }
  if (tag.fontSize) {
    styles.icon.fontSize = `${tag.fontSize} !important`
  }

  return { styles, iconClass }
}
