import angular from 'angular'
import * as storage from '@app/utils/storage'
import template from './mobileAppPromotionBanner.html'

export default angular
  .module('MobileAppPromotionBannerComponentModule', [])
  .directive('mobileAppPromotionBanner', function () {
    return {
      scope: {},
      template,
      controller($scope, config, mobileService, utilService) {
        const storageKey = 'hideMobilePromotionBanner'
        // eslint-disable-next-line import/no-named-as-default-member
        const storageService = storage.getInstance()

        $scope.mobile = mobileService
        $scope.hasBannerBeenHidden = true
        $scope.isBannerEnabled = false
        $scope.config = config
        $scope.util = utilService
        // eslint-disable-next-line no-unused-expressions
        $scope.bannerImageUrl
        // eslint-disable-next-line no-unused-expressions
        $scope.appStoreLink
        checkForBannerHideFlag()
        setAppStoreLink()

        $scope.closeBanner = function () {
          storageService.setItem(storageKey, true)
          $scope.hasBannerBeenHidden = true
        }

        function setAppStoreLink() {
          const mobileOs = mobileService.getMobileOs()
          if (
            mobileOs &&
            config.appStoreLinks &&
            config.appStoreLinks[mobileOs] &&
            config.mobileAppPromotionBannerImageUrl[mobileOs]
          ) {
            $scope.appStoreLink = config.appStoreLinks[mobileOs]
            $scope.bannerImageUrl = config.mobileAppPromotionBannerImageUrl[mobileOs]

            // Enable banner when a proper app store link and banner is configured for OS/App type combination.

            $scope.isBannerEnabled = true
          }
        }

        function getLegacyCookieValue() {
          const value = `; ${document.cookie}`
          const parts = value.split(`; ${storageKey}=`)
          // eslint-disable-next-line eqeqeq
          if (parts.length == 2) return parts.pop().split(';').shift()
        }

        function checkForBannerHideFlag() {
          storageService.getItem(storageKey).then(value => {
            // Show banner if no value found in storage and cookie
            // TODO: Remove cookie check once the new storage method has been
            // running for a release cycle
            if (!value && !getLegacyCookieValue()) {
              $scope.hasBannerBeenHidden = false
            }
          })
        }
      },
    }
  })
