import angular from 'angular'
import dayjs from 'dayjs'
import { DEFAULT_TIMESLOT_DATE_FORMAT } from '@app/angular/constants/timeslots'
import { utils as timeslotUtils } from '@instacart/enterprise-timeslots'

export default angular
  .module('ReservationConfirmationControllerModule', [])
  .controller(
    'ReservationConfirmationController',
    function (
      $injector,
      $interval,
      $q,
      $rootScope,
      $scope,
      $state,
      $timeout,
      addressList,
      auth,
      cartService,
      checkoutService,
      compareCartService,
      config,
      errorModalService,
      reservationService,
      shoppingSelectorService,
      storesService
    ) {
      $scope.config = config
      $scope.inCheckout = $state.includes('main.checkout')
      $scope.shoppingSelectorService = shoppingSelectorService
      $scope.shoppingIntent = auth.getContext().intent
      $scope.compareCartService = compareCartService
      $scope.showMultipleDeliveryAddresses =
        $scope.shoppingIntent === 'delivery' && config.enableMultipleDeliveryAddresses

      $scope.showHomeStore = $scope.shoppingIntent !== 'delivery'
      $scope.showDeliveryFees =
        $scope.shoppingIntent === 'delivery' &&
        !config.shoppingSelector.delivery.fulfilledByInstacart
      $scope.handleProfileThroughGigya = auth.shouldUseGigyaAddressEdit()
      $scope.deliveryAddress = {}
      $scope.isReservationExpired = false

      if ($scope.showMultipleDeliveryAddresses) {
        addressList.fetchAddressPromise.then(() => {
          $scope.deliveryAddress = addressList.getAddressFromId(auth.getContext().address_id)
        })
      } else {
        $scope.deliveryAddress = auth.user.delivery_address
      }

      const gigyaService = $scope.handleProfileThroughGigya ? $injector.get('gigyaService') : null

      $scope.handleAddressEdit = () => {
        gigyaService.gigyaCheckoutAddress()
      }

      // If the user arrives without a reservation, open the timeslot selection modal by default
      if (!reservationService.reservation) {
        shoppingSelectorService.open('timeslot')
      }

      const reservationInterval = $interval(() => {
        if (!reservationService.reservation) {
          $scope.isReservationExpired = true
          return
        }

        const expirationInSeconds = dayjs(reservationService.reservation.expiration)
          .utc()
          .diff(dayjs().utc(), 'seconds')

        if (expirationInSeconds >= 0 || cartService.cart.order) {
          $scope.isReservationExpired = false
          return
        }

        reservationService.deleteReservation().then(() => {
          $timeout(() => shoppingSelectorService.open('timeslot'), 1000)
        })
        $scope.isReservationExpired = true
      }, 1000)

      // Format the expiration time based

      $scope.expiryLabel = function () {
        if (!reservationService.reservation) {
          return ''
        }

        const minutes = dayjs(reservationService.reservation.expiration)
          .utc()
          .diff(dayjs().utc(), 'minutes')
        const seconds = dayjs(reservationService.reservation.expiration)
          .utc()
          .diff(dayjs().utc(), 'seconds')

        if (minutes > 60) {
          return `${Math.floor(minutes / 60)} hours ${minutes % 60} minutes`
        }

        if (seconds <= 0) {
          return 'Your time slot is expired'
        }
        return `Your time slot will expire in ${minutes} minutes and ${seconds % 60} seconds`
      }

      const handleStoreNotInListError = (store, postalCode) => {
        const modalTitle = `
        ${config.lang.storeSuggestionError}
        <br /><br />
        ${store.name}
      `

        errorModalService.open(modalTitle, 'Yes', 'No').then(() => {
          compareCartService
            .compareCart(true, 'store', { store_id: store.id, delivery_postal_code: postalCode })
            .then(() => {
              // Update context to new store and then proceed to reservation confirmation
              $q.all([
                auth.update({ store_id: store.id }),
                auth.setContext({ deliveryPostalCode: postalCode }),
              ])
                .then($rootScope.reloadState) // store has changed update cart, reservation, etc.
                .then(() => {
                  $state.go('main.checkout.reservation.confirmation')
                })
            })
        })
      }

      const handleDeliveryValidation = ({ isValid, errorType }, successCallback) => {
        if (isValid) {
          return successCallback()
        }

        switch (errorType) {
          case 'NO_STORES_FOUND':
            errorModalService.open(config.lang.noDeliveryToPostalCodeError, 'OK')
            break
          case 'STORE_NOT_IN_THE_LIST': {
            const suggestedStore = storesService.stores[0]
            const postalCode = auth.user.delivery_address.postal_code
            handleStoreNotInListError(suggestedStore, postalCode)
            break
          }
          default:
            // eslint-disable-next-line no-throw-literal
            throw `Error: unknown validateDelivery error type: ${errorType}`
        }
      }

      $scope.timeslotLabelFn = () =>
        timeslotUtils.getMainLabel($scope.reservationService.reservation, {
          verboseLevel: 1,
          format: $scope.reservationService.reservation
            ? config.apiDateFormat
            : DEFAULT_TIMESLOT_DATE_FORMAT,
        })

      $scope.toPayment = function () {
        const successCallback = () => {
          if (
            ($scope.showMultipleDeliveryAddresses &&
              addressList.isDeliveryAddressComplete($scope.deliveryAddress)) ||
            (!$scope.showMultipleDeliveryAddresses &&
              auth.validateFulfillmentContactInfo(
                reservationService.reservation.timeslot.fulfillment_type,
                auth.user
              ))
          ) {
            $scope.compareCartService.compareCart(true, 'reservation').then(() => {
              $state.go('main.checkout.payment')
              checkoutService.setDeliveryAddress($scope.deliveryAddress)
            })
          } else {
            errorModalService.open('Please complete your contact information.', 'OK')
          }
        }

        // If Gigya is enabled and the context is "delivery" we do address validation here
        if (config.gigya && config.gigya.enabled && auth.user.context.intent === 'delivery') {
          storesService
            .validateDelivery($scope.deliveryAddress.postal_code, auth.user.store.id)
            .then(res => {
              handleDeliveryValidation(res, successCallback)
            })
        } else {
          successCallback()
        }
      }

      $rootScope.$on('$stateChangeStart', (event, toState, toParams, fromState) => {
        if (fromState.name === 'main.checkout.reservation.confirmation') {
          $interval.cancel(reservationInterval)
        }
      })
    }
  )
