import React, { memo, forwardRef } from 'react'
import PropTypes from 'prop-types'
import { createStyles, utils, LazyImage } from '@instacart/cocktail'

const useStyles = createStyles(
  tokens => ({
    root: {
      width: '100%',
      borderTop: utils.toPx`${tokens.border.width.regular} solid ${tokens.color.border.regular}`,
    },
  }),
  { displayName: 'SubscriptionCheckoutFooter' }
)

const Footer = forwardRef((props, ref) => {
  const styles = useStyles(props)
  const { imageUrl, i18n, assetLoadingFunction, className } = props

  return (
    <div ref={ref} css={styles.root} className={className}>
      <LazyImage
        src={assetLoadingFunction(imageUrl)}
        alt={i18n.imgAltText}
        width="100%"
        height="100%"
      />
    </div>
  )
})

Footer.defaultProps = {
  assetLoadingFunction: url => url,
}

Footer.propTypes = {
  /** Custom style class name for the containing element */
  className: PropTypes.string,
  /** Image to show in the footer */
  imageUrl: PropTypes.string,
  /** Footer image alt text */
  i18n: PropTypes.shape({
    imgAltText: PropTypes.string,
  }).isRequired,
  /** function to load the asset images, this is needed for webpack to bundle the assets */
  /** TODO: this can be removed once we move the images to a cdn and they don't have to be bundled */
  assetLoadingFunction: PropTypes.func,
}

export default memo(Footer)
