import React, { Fragment, memo, useState } from 'react'
import PropTypes from 'prop-types'
import { PaymentFormVantiv, CARD_PROPS } from '@instacart/enterprise-payments'
import { createStyles, Alert, utils } from '@instacart/cocktail'
import { SUBSCRIPTION_PATHS } from '../constants'
import Header from '../Header'

const useStyles = createStyles(tokens => ({
  root: {
    paddingLeft: tokens.size.spacing.medium,
    paddingRight: tokens.size.spacing.medium,
  },
  isAddNewCardRoot: {
    paddingLeft: 0,
    paddingRight: 0,

    [utils.mediaQuery.minWidth(tokens.breakpoint.small)]: {
      paddingLeft: tokens.size.spacing.medium,
      paddingRight: tokens.size.spacing.medium,
    },
  },
  error: {
    marginBottom: tokens.size.layout.xSmall,
  },
  header: {
    padding: tokens.size.spacing.medium,
    marginBottom: tokens.size.spacing.xxLarge,
  },
}))

const defaultLang = {
  defaultErrorMessage: 'Oops! Something went wrong... Please try again.',
  criticalErrorMessage: 'Oops! Something went wrong... Please contact support.',
}

/**
 * @Component
 * SubscriptionPayment
 *
 * This handles displaying of payment options and the purchasing
 */
const SubscriptionPayment = props => {
  const {
    cards,
    email,
    i18n,
    isAddNewCard,
    isIframeLoading,
    customMessage,
    onAddNewCard,
    onPurchaseComplete,
    onStepComplete,
    path,
    selectedSubscription,
    services,
  } = props

  const styles = useStyles(props)
  const { loadingService, subscriptionsService } = services
  const { isLoading } = loadingService.selectors
  const { BUY_SUBSCRIPTION, GIFT_SUBSCRIPTION } = subscriptionsService.constants
  const [errorMessage, setErrorMessage] = useState('')
  const showLoading = isIframeLoading || isLoading(BUY_SUBSCRIPTION) || isLoading(GIFT_SUBSCRIPTION)
  const lang = { ...defaultLang, ...i18n }

  const purchaseSubscription = async paymentInstrumentId => {
    const card = cards.find(({ ext_id }) => ext_id === paymentInstrumentId)
    let errorLang = lang.defaultErrorMessage

    try {
      const subscriptionId = selectedSubscription.id

      switch (path) {
        case SUBSCRIPTION_PATHS.BUYING:
          await subscriptionsService.promiseActions.buySubscription(subscriptionId, {
            payment_instrument: card,
          })
          break
        case SUBSCRIPTION_PATHS.GIFTING:
          await subscriptionsService.promiseActions.giftSubscription(
            subscriptionId,
            { payment_instrument: card },
            { email, message: customMessage }
          )
          break
        default:
          errorLang = lang.criticalErrorMessage
          throw new Error(`Unrecognized subscription path: ${path}`)
      }

      onPurchaseComplete({ selectedSubscription, path, email })
      onStepComplete()
    } catch (e) {
      setErrorMessage(errorLang)
      throw e
    }
  }

  return (
    <Fragment>
      <Header css={styles.header} i18n={i18n.header[path] || i18n.header.default} />
      <div css={[styles.root, isAddNewCard && styles.isAddNewCardRoot]}>
        {errorMessage && (
          <Alert variant="error" css={styles.error}>
            {errorMessage}
          </Alert>
        )}
        <PaymentFormVantiv
          {...props}
          id="subscriptionPaymentFormVantiv"
          cards={cards || []}
          isIframeLoading={showLoading || !cards}
          onSubmitCard={purchaseSubscription}
          onAddNewCard={() => {
            setErrorMessage('')
            return onAddNewCard()
          }}
        />
      </div>
    </Fragment>
  )
}

SubscriptionPayment.propTypes = {
  /** List of cards */
  cards: PropTypes.arrayOf(CARD_PROPS),
  /** For gifting: The email to send the gift to */
  email: PropTypes.string,
  /** Language overrides */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Whether to show the new card iframe or not */
  isAddNewCard: PropTypes.bool,
  /** Whether to show the loader or not */
  isIframeLoading: PropTypes.bool,
  /** For gifting: The message to send with the gift */
  customMessage: PropTypes.string,
  /** Path: Determine if we're gifting or buying */
  path: PropTypes.string.isRequired,
  /** function to call when the step is complete */
  onPurchaseComplete: PropTypes.func,
  /** function to call when the step is complete */
  onStepComplete: PropTypes.func,
  /** function to add a new card - used for testing the readyness of Angular JS */
  onAddNewCard: PropTypes.func,
  /** The selected subscription from the previous step */
  selectedSubscription: PropTypes.shape({
    id: PropTypes.number.isRequired,
  }).isRequired,
  /** Services for use with SubscriptionCheckout */
  services: PropTypes.shape({
    // eslint-disable-next-line react/forbid-prop-types
    loadingService: PropTypes.object.isRequired,
    // eslint-disable-next-line react/forbid-prop-types
    subscriptionsService: PropTypes.object.isRequired,
  }).isRequired,
  /** Styles to override the default styles */
  // eslint-disable-next-line react/forbid-prop-types
  styles: PropTypes.object,
}

SubscriptionPayment.defaultProps = {
  onAddNewCard: () => {},
  onPurchaseComplete: () => {},
  onStepComplete: () => {},
  customMessage: '',
}

export default memo(SubscriptionPayment)
