import _ from 'lodash'
import angular from 'angular'
import { ADDRESS_FIELDS } from '@app/angular/constants/address'

import template from './addressList.html'

export default angular.module('AddressListComponentModule', []).component('addressList', {
  bindings: {
    selectableAddresses: '@',
    selectAddressCallback: '&',
    editAddressCallback: '&',
    currentAddress: '<',
    validateAddress: '<',
    /**
     * Boolean flag wether to display the new address entry form,
     * or hide it and leave it to the user to click the "Add new address" button
     **/
    hideAddressFormOnLoad: '<',
    showSaveMessages: '<',
    /** Boolean flag wether to display some idle message when there are no address entries */
    showIdleMessage: '<',
    /** An optional message that will appear on the top left of the list of addresses */
    textHeader: '<',
    optionalFields: '<?',
  },
  template,
  controller: function (
    $rootScope,
    $scope,
    $state,
    $timeout,
    addressList,
    addressValidationService,
    analytics,
    auth,
    config,
    errorModalService,
    lang
  ) {
    const vm = this

    Object.assign(vm, {
      lang,
      addressList,
      config,
      profile: null,
      successMessage: '',
      selectedAddress: null,
      currentEditingId: null,
      currentEditingItem: null,
      editableAddresses: !vm.selectableButtons,
      selectableRadios: vm.selectableAddresses === 'radio',
      selectableButtons: vm.selectableAddresses === 'button',
    })

    vm.$onInit = () => {
      vm.optionalFields = vm.optionalFields || []
    }

    const selectAddressFromCurrentAddress = address => {
      if (address.id) {
        const addressItem = _.find(vm.addressList.addressItems, o => o.id === address.id)
        if (addressItem) {
          return addressItem
        }
      }

      let filteredAddresses = vm.addressList.addressItems
      let selectedAddress
      const ADDRESS_KEYS = [
        ADDRESS_FIELDS.POSTAL_CODE,
        ADDRESS_FIELDS.CITY,
        ADDRESS_FIELDS.ADDRESS_LINE_ONE,
        ADDRESS_FIELDS.ADDRESS_LINE_TWO,
        ADDRESS_FIELDS.LAST_NAME,
        ADDRESS_FIELDS.FIRST_NAME,
        ADDRESS_FIELDS.PHONE_NUMBER,
        ADDRESS_FIELDS.INSTRUCTIONS,
      ]
      for (let i = 0; i < ADDRESS_KEYS.length; i++) {
        const key = ADDRESS_KEYS[i]
        if (address[key] && filteredAddresses.length) {
          filteredAddresses = _.filter(filteredAddresses, o => o[key] === address[key])
          if (filteredAddresses && filteredAddresses.length) {
            selectedAddress = filteredAddresses[0]
            if (filteredAddresses.length === 1) {
              return selectedAddress
            }
          }
        }
      }
      return selectedAddress
    }

    const prefillNewAddress = () => {
      const { id, ...addressWithoutId } = vm.currentAddress || {}
      // null index means there's no item being currently edited and the form should not be shown
      const initialIndex = vm.hideAddressFormOnLoad ? null : -1
      vm.editAddressItem(initialIndex, addressWithoutId)
    }

    vm.getAddressItems = () => {
      return addressList.getAddresses().then(response => {
        if (vm.addressList.addressItems.length === 0) {
          // Open the New address form if there are no saved addresses.
          prefillNewAddress()
        }
      })
    }

    vm.getAddressItems().then(() => {
      if (vm.selectableRadios && vm.currentAddress) {
        // Set the correct selected address id.
        const selectedAddress = selectAddressFromCurrentAddress(vm.currentAddress)
        if (selectedAddress && selectedAddress.id) {
          vm.selectedAddress = selectedAddress.id

          if (!addressValidationService.isAddressComplete(selectedAddress, vm.optionalFields)) {
            vm.editAddressItem(vm.selectedAddress)
            return
          }

          vm.onAddressSelected(selectedAddress)
        } else if (vm.currentAddress && vm.currentAddress.postal_code) {
          // Prefill new address if none of the existing ones match.
          prefillNewAddress()
        }
      }
    })

    const _mergeProfileToAddress = (profile, address) => {
      const { first_name, last_name, phone_number } = profile
      return { first_name, last_name, phone_number, ...address }
    }

    const _handleProfileFetch = profile => {
      const { context = {}, first_name, last_name, primary_number_key } = profile
      const phone_number = profile[primary_number_key]

      return {
        address1: context.street_address,
        city: context.city,
        first_name,
        last_name,
        phone_number,
        province: context.province,
        postal_code: context.deliveryPostalCode,
      }
    }

    const _setProfile = profile => {
      vm.profile = profile
      vm.currentEditingItem = _mergeProfileToAddress(profile, vm.currentEditingItem)
    }

    vm.editAddressItem = (itemId, initialAddress) => {
      // Prevent this action if user is modifying IC order
      if ($rootScope.modifyingInstacartOrder) return

      vm.resetSuccessMessage()
      vm.currentEditingId = itemId

      // Add new address
      if (itemId === -1) {
        vm.currentEditingItem = initialAddress || {}
        if (vm.profile) {
          // prefill first name, last name and phone number from profile
          vm.currentEditingItem = _mergeProfileToAddress(vm.profile, vm.currentEditingItem)
        } else {
          auth.getProfile().then(_handleProfileFetch).then(_setProfile)
        }
      } else if (itemId === null) {
        vm.currentEditingItem = null
      } else {
        vm.currentEditingItem = angular.copy(
          _.find(vm.addressList.addressItems, o => o.id === itemId)
        )
      }

      vm.editingAddress(vm.currentEditingItem)
    }

    $scope.$on('editAddressItem', (event, { itemId, initialAddress }) =>
      vm.editAddressItem(itemId, initialAddress)
    )

    vm.editingAddress = address => {
      vm.editAddressCallback && vm.editAddressCallback({ address })
    }

    vm.cancelAddressEdit = () => {
      const index = vm.addressList.addressItems.findIndex(item => item.id === vm.currentEditingId)
      vm.editAddressItem(null)

      $timeout(() => {
        document
          .getElementById('saved-address-list')
          .querySelectorAll('.address-list-item-action-edit')
          [index]?.focus()
      })
    }

    vm.selectedLoading = false
    vm.onAddressSelected = (address, saveSelectedAddress) => {
      if (vm.selectAddressCallback) {
        vm.selectedLoading = true
        // The return can sometimes be a promise or null.
        // Therefore, try to resolve the promise before turning off the loading wheel.
        try {
          vm.selectAddressCallback({ address, saveSelectedAddress }).finally(() => {
            vm.selectedLoading = false
          })
        } catch (TypeError) {
          vm.selectedLoading = false
        }
      }
    }

    vm.onAddressSave = address => {
      vm.setSuccessMessage()
      vm.editAddressItem(null)
      // Select the address that just got saved if radio inputs are shown.
      if (vm.selectableAddresses && address.id) {
        vm.selectedAddress = address.id
        vm.onAddressSelected(address, true)
      }
    }

    vm.deleteAddressItem = itemId => {
      // Prevent this action if user is modifying IC order
      if ($rootScope.modifyingInstacartOrder) return

      vm.resetSuccessMessage()
      errorModalService
        .open(
          lang.values.addressDeleteConfirmation,
          lang.values.yes,
          lang.values.cancel,
          lang.values.addressDelete
        )
        .then(() => {
          addressList
            .deleteAddress(itemId)
            .then(response => {
              vm.editAddressItem(null)
              vm.setSuccessMessage('Address deleted successfully!')
              // Unselect the current address if it's the one that was deleted.
              if (vm.selectedAddress === itemId) {
                vm.selectedAddress = null
                vm.onAddressSelected({})
              }
              return response
            })
            .catch(response => {
              if (response.error === 40304) {
                registrationService.promptLogin(
                  $state.current.name,
                  '',
                  '',
                  '',
                  lang.values.loginToContinue
                )
              } else {
                vm.error = response.data ? response.data.message : response.message
              }
            })
        })
    }

    vm.setSuccessMessage = message => (vm.successMessage = message || 'Address saved successfully!')
    vm.resetSuccessMessage = () => (vm.successMessage = '')
  },
})
