import React, { Suspense } from 'react'
import PropTypes from 'prop-types'
import { Alert } from '@instacart/cocktail'
import { ErrorBoundary } from '@instacart/enterprise-ui-common'

import TimeslotSelectLoading from './TimeslotSelectLoading'

const defaultLang = {
  timeslotsError: 'It was not possible to retrieve timeslots.',
}

const TimeslotsContainer = ({ children }) => {
  const lang = { ...defaultLang }

  return (
    <ErrorBoundary
      fallback={
        <Alert variant="error" data-test="timeslot-select-error-alert">
          {lang.timeslotsError}
        </Alert>
      }
    >
      <Suspense fallback={<TimeslotSelectLoading />}>{children}</Suspense>
    </ErrorBoundary>
  )
}

TimeslotsContainer.propTypes = {
  children: PropTypes.node.isRequired,
}

export default TimeslotsContainer
