import _ from 'lodash'
import angular from 'angular'

import { product as ProductService } from '@instacart/enterprise-services'
import { PRODUCT_CELL_OFFER_TYPES } from '@app/angular/constants/offers'
import tokens from '@app/angular/services/modules/tokens'
import {
  adjustDollarsToCents,
  concatProductTitle,
  getProductOfferPoints,
  getUnclippedCoupons,
  getProductCellBadgeClassName,
  getProductPriceLabel,
  getOverlayTriggerChance,
  getEnterpriseSaleEndDate,
} from '@app/utils/productHelpers'

export default angular.module('ProductCellNgControllerModule', []).controller(
  'ProductCellNgController',
  // eslint-disable-next-line complexity
  function (
    $document,
    $element,
    $filter,
    $rootScope,
    $scope,
    $state,
    $timeout,
    Restangular,
    analytics,
    auth,
    cartService,
    config,
    configurableProductService,
    eComLightService,
    facetedSearchService,
    imageURLService,
    lang,
    mobileService,
    offersService,
    productDetailsModal,
    productsService,
    tagsService,
    userService
  ) {
    let customBadge = null
    const product = $scope.product || {}
    const productConfig = product.product_config || {}

    // adjust sale and loyalty end date for IC catalog items due to logic
    // between IC and Unata
    product.sale_end_date = getEnterpriseSaleEndDate(product.sale_end_date, config)
    product.loyalty_end_date = getEnterpriseSaleEndDate(product.loyalty_end_date, config)

    $scope.isProductAvailable = ProductService.utils.isProductAvailable($scope.product)

    // Services
    $scope.imageURLService = imageURLService
    $scope.tagsService = tagsService
    $scope.mobile = mobileService
    $scope.lang = lang
    $scope.facetedSearchService = facetedSearchService

    // General defaults
    $scope.overlay = {}
    $scope.config = config
    $scope.showAllOptions = false
    const enableFacets = config.enableFacetedSearchFilters || config.enableICSearch

    // Variant
    $scope.showQuickAdd = false
    $scope.isMobileApp = mobileService.isMobileApp()

    // Flags
    const {
      enableCustomOfferBadges,
      enableIntegrationOfferBadges,
      enableCustomOfferInsideBMSMBadge,
      enableLoyaltyPricing,
    } = config

    // Offers
    const { offers } = product
    $scope.offerType = null
    $scope.offerConditionAndReward = {}
    $scope.OFFER_TYPES = PRODUCT_CELL_OFFER_TYPES
    $scope.hasOffer = !!offers
    const hasOneOffer = $scope.hasOffer && offers.length === 1

    // Offer Points
    $scope.offerPoints = $scope.hasOffer ? getProductOfferPoints(offers) : null
    $scope.hasOfferPoints = !!$scope.offerPoints

    $scope.showOfferBadge =
      $scope.hasOffer &&
      (!$scope.hasOfferPoints || !config.hasLoyaltyPoints) &&
      config.enableOfferBadge &&
      config.enableOfferBadgeTypes.includes(product.offers[0].offer_type)

    $scope.offerBadgeAltText = lang.aria.offerBadge || 'Offer inside!'

    // Show a custom badge if one is present in the offer ext_data
    if (enableIntegrationOfferBadges && product.offers) {
      // This is RAY specific (and likely always will be) filter out the product offers
      // with badges from the full set & the product offers that they wish to hide the
      // badges on from the full set
      const productOffersWithBadges = product.offers.filter(product => product.badge_type !== null)
      const hiddenProductOffers = product.offers.filter(product => !product.display_badge)

      if (product?.offers?.length === 1) {
        // If we only have one offer, hide the badge if they are passing dsiplay_badge=false
        // otherwise render the badge as passed via API
        if (!product.offers[0].display_badge) {
          customBadge = 'hidden'
        } else if (product.offers[0].badge_type) {
          customBadge = product.offers[0].badge_type
          $scope.offerBadgeAltText = `${product.offers[0].badge_type} offer`
        }
      } else {
        // If we have multiple offers, check if all of the offers present are display_badge=False,
        // if so - hide the badge entirely, if not and there is a single badge on the product render that
        // badge, otherwise defer the render logic to the getProductCellBadgeClassName helper by passing null
        // eslint-disable-next-line no-lonely-if
        if (
          hiddenProductOffers.length > 0 &&
          hiddenProductOffers.length === product?.offers?.length
        ) {
          customBadge = 'hidden'
        } else if (productOffersWithBadges.length === 1) {
          customBadge = productOffersWithBadges[0].badge_type
          $scope.offerBadgeAltText = `${productOffersWithBadges[0].badge_type} offer`
        } else {
          customBadge = null
        }
      }
    }

    // We should show a custom badge if there is not more than one offer on a product & conditions are met
    if (enableCustomOfferBadges && hasOneOffer) {
      offersService.getOffer(product.offers[0].id).then(result => {
        // Checks
        const isConditionTypeProducts = result.condition.type === 'products'
        const isRewardTypeProducts = result.reward.type === 'products'
        const isRewardTypeDiscount = result.reward.type === 'discount'

        // Result values
        const resultReward = result.reward[result.reward.type]
        const resultCondition = result.condition[result.condition.type]

        const setOffer = ({ badge, condition, type }) => {
          if (type) $scope.offerType = type
          if (badge) customBadge = badge
          if (condition) $scope.offerConditionAndReward = condition
        }

        if (enableCustomOfferInsideBMSMBadge && resultCondition.max_quantity !== null) {
          setOffer({
            badge: 'custom-offer',
            type: $scope.OFFER_TYPES.BMSM,
          })
        } else if (isConditionTypeProducts && isRewardTypeDiscount) {
          // Potentially a $ off or % off offer
          if (resultReward.type === 'amount' && result.source === 'internal') {
            setOffer({
              badge: 'custom-offer',
              condition: {
                rewardAmount: adjustDollarsToCents(resultReward.value),
              },
              type: $scope.OFFER_TYPES.AMOUNT_REWARD,
            })
          } else if (resultReward.type === 'percent') {
            setOffer({
              badge: 'custom-offer',
              condition: {
                rewardAmount: resultReward.value,
              },
              type: $scope.OFFER_TYPES.PERCENT_REWARD,
            })
          }
        } else if (isConditionTypeProducts && isRewardTypeProducts) {
          setOffer({
            badge: 'custom-offer',
            condition: {
              conditionAmount: resultCondition.quantity,
              rewardAmount: resultReward.quantity,
            },
            type: $scope.OFFER_TYPES.BUY_X,
          })
        }
      })
    }

    const unclippedCoupons = $scope.hasOffer ? getUnclippedCoupons(product.offers) : []

    $scope.hasUnclippedCoupon = unclippedCoupons.length > 0

    $scope.showAisle = !$scope.config.disableAisles && product.aisle

    $scope.hasLoyaltyCard = () => userService.loyaltyCards.length > 0

    $scope.showLoyaltyRibbon = () =>
      enableLoyaltyPricing && product.promo_tag && product.loyalty_price

    $scope.showSaleRibbon = () => product.promo_tag && !$scope.showLoyaltyRibbon()

    $scope.showOfferPreviewRibbon = () => product.offer_title && product.offer_title.length > 0

    // Grabs copy for sponsered by text from config
    $scope.sponsoredByText =
      product.sponsored && product.sponsoredBy
        ? lang.values.sponsoredBy[product.sponsoredBy]
        : undefined

    $scope.showFeaturedLabel =
      $scope.promotedProduct && config.showFeaturedLabel && !$scope.sponsoredByText

    $scope.showSponsoredLabel =
      product.sponsored &&
      config.showSponsoredLabel &&
      !$scope.showFeaturedLabel &&
      !$scope.sponsoredByText

    // If the API response determines the sponsored label should be hidden, hide it
    // This property doesn't exist yet, so check if the value is explicitly set to false
    // Otherwise, use the logic above
    if (product.sponsoredLabel === false) {
      $scope.showSponsoredLabel = false
    }

    $scope.productCellBadge = () =>
      getProductCellBadgeClassName({
        customBadge,
        hasOffer: $scope.hasOffer,
        hasOfferPoints: $scope.hasOfferPoints,
        hasLoyaltyPoints: config.hasLoyaltyPoints,
        enableCustomOfferInsideBadge: config.enableCustomOfferInsideBadge,
      })

    // "From" text is shown when there is a custom configurable product that has pricing associated with it.
    $scope.priceLabel = getProductPriceLabel(productConfig, lang)

    $scope.priceStyles = { root: { fontSize: tokens.size.font.caption - 1 } }

    // Similar to the `update` function in `addToCartButton.js`, this method fires when there is change to cart
    // So that we can update the Add To Cart button accordingly to reflect any additions/removals
    // This has to be executed in `productCell` to support the configurable products popover on addToCartButton
    const updateForConfigurations = function (cleared) {
      if (productConfig.options?.length > 0 && productConfig.config_type === 'prep_style') {
        let selectedConfiguration = []

        $scope.aggregateQuantityCount = 0
        $scope.existingConfigurationCartItems = []
        $scope.existingConfigSingular = null

        // No default options available, show all options on Add To Cart popover
        $scope.showAllOptions = !configurableProductService.findDefault(
          productConfig.options[0].items
        )

        // Checks all product's available configs and sees if they exist in cart
        productConfig.options[0].items.forEach(option => {
          selectedConfiguration = configurableProductService.formatConfigForCart(product, option)

          // Find configuration in cart and update quantity counts and aggregates in-cart configurations
          // To pass to addToCartButton popover
          const item = !cleared && cartService.getItem(product, selectedConfiguration)
          // TODO - remove lodash (probably rewrite this) when I can find out where this is used.
          if (item && !_.find($scope.existingConfigurationCartItems, item)) {
            $scope.aggregateQuantityCount += item.quantity
            $scope.existingConfigurationCartItems.push(item)

            if ($scope.existingConfigurationCartItems.length === 1) {
              $scope.aggregateQuantityCount = item.quantity
              $scope.existingConfigSingular = item
            } else {
              $scope.existingConfigSingular = null
            }
          }
        })
      }
    }

    /*
     * COMPONENTS
     */
    // Title
    $scope.productTitle = () => concatProductTitle(product, lang)

    // Price Alerts

    // Catch price alert changes from a modal
    // TODO: This needs to be refactored, we shouldn't need to $emit to capture scope.
    $rootScope.$on('priceAlertChange', (event, { productId, priceAlert }) => {
      if (productId === product.id) {
        product.price_alert = priceAlert
      }
    })

    // Product Details can be a modal or a regular page.
    $scope.linkToProductDetails = () => {
      productsService.cellOnClick({
        location: $scope.location,
        locationValue: $scope.locationValue,
        modal: $scope.productDetailsModalLink,
        onAddToCartBeacon: product.onAddToCartBeacon,
        product,
        placement: {
          promoted: $scope.promotedProduct,
          source: product.sponsoredBy,
          isExternalRoutesDisabled: $scope.isExternalRoutesDisabled,
        },
      })
    }

    const overlayTriggerChance = getOverlayTriggerChance(
      $scope.hasUnclippedCoupon,
      config.productCellCouponChance,
      config.productCellRecoChance
    )

    if (!config.disableProductTileProductReco && !eComLightService.inEComLightMode) {
      const deregister = $scope.$on('productAdded', (name, addedProduct) => {
        if (addedProduct.id === product.id && overlayTriggerChance) {
          if ($scope.hasUnclippedCoupon && $state.current.name !== 'main.offers.details') {
            offersService.getOfferData(unclippedCoupons[0].id).then(argument => {
              $scope.overlay.featuredCoupon = argument.offer
            })
          } else {
            Restangular.one('store_products', addedProduct.id)
              .all('related')
              .getList({ limit: 1 })
              .then(response => {
                $scope.overlay.recoProduct = response.plain()[0]
              })
          }
        }
        updateForConfigurations()
      })
      $scope.$on('$destroy', () => {
        deregister()
      })
    }

    // Watchers to update configurable dropdown
    if (productConfig.options?.length > 0 && productConfig.config_type === 'prep_style') {
      const deregisterGroupWatcher = $scope.$watchGroup(
        ['cartService.lastUpdated', 'product'],
        updateForConfigurations
      )
      const deregisterCartServiceWatcher = $scope.$on('cartService', (event, method, item) => {
        const cartChange =
          ['add', 'remove'].includes(method) && product.id === item?.store_product?.id
        const cartCleared = method === 'clearConfirm'
        if (cartChange || cartCleared) {
          updateForConfigurations(cartCleared)
        }
      })

      if (cartService.cart) {
        updateForConfigurations()
      } else {
        cartService.fetch().then(updateForConfigurations)
      }

      $scope.$on('$destroy', () => {
        deregisterGroupWatcher()
        deregisterCartServiceWatcher()
      })
    }

    $scope.storeImpressionIfFeatured = () => {
      if (product.placement) analytics.setFeaturedProductImpressionToStore(product.id)
    }

    $scope.closeQuickAdd = (event, forceClose) => {
      const el = $element[0]

      // Detect where the element is clicked so that buttons inside don't close the overlay
      if ((el !== event?.target && !el.contains(event?.target)) || forceClose) {
        $scope.showQuickAdd = false
        $document.off('click', $scope.closeQuickAdd)

        // Force refresh
        $timeout(() => $scope.$apply())
      }
    }

    $scope.onQuickAddClick = () => {
      // Hack to get around binding issue
      $timeout(() => {
        $scope.showQuickAdd = true
        const el = document.getElementById(`add-to-cart-modify-add-button-${$scope.product.id}`)

        if (el) {
          // Hack to get around waiting for element to be visible
          // Focus the add to cart button so the user can continue to add to cart
          $timeout(() => {
            el.focus()
          }, 10)
        }

        $document.on('click', $scope.closeQuickAdd)
      })
    }

    $scope.offerBadgeAriaRole = () => (!$scope.offerType ? 'img' : undefined)
    $scope.offerBadgeAriaLabel = () => (!$scope.offerType ? $scope.offerBadgeAltText : undefined)

    $scope.getTagName = tag => {
      return enableFacets ? facetedSearchService.getTagName(tag) : tagsService.byVal(tag)?.name
    }

    productsService.trackViewPort($element, $scope, product)
  }
)
