import _ from 'lodash'
import angular from 'angular'
import { CART_TYPES } from '@app/angular/constants/cart'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { IC_PLACEMENTS } from '@app/angular/constants/express'
import {
  product as enterpriseProductService,
  shoppingList as shoppingListService,
} from '@instacart/enterprise-services'
import { EVENTS } from '@app/angular/services/modules/analytics/analyticsConstants'
import tokens from '@app/angular/services/modules/tokens'
import { getDataByCartType } from '@app/react/components/miniCart/utils'
import alcoholValidationErrorModalTemplate from '../../services/templates/alcoholValidation/alcoholValidationErrorModal.html'
import unableToOpenPDP from './views/unableToOpenPDP.html'

export default angular
  .module('CheckoutCartControllerModule', [])
  .controller(
    'CheckoutCartController',
    function (
      $anchorScroll,
      $filter,
      $q,
      $rootScope,
      $sce,
      $scope,
      $state,
      Restangular,
      alcoholValidation,
      analytics,
      api,
      auth,
      cartService,
      catalogCategoriesService,
      checkoutAnalytics,
      config,
      configurableProductService,
      confirmationModal,
      couponUnclippedModalService,
      eComLightService,
      errorModalService,
      express,
      imageURLService,
      lang,
      listPrintService,
      loyaltyService,
      manualUpsellService,
      mobileNativeHrefService,
      mobileService,
      navService,
      offersService,
      productDetailsModal,
      promoCodeValidationService,
      reactCartLoyaltyBalanceDefaultProps,
      reservationService,
      shoppingSelectorService,
      upsellProducts,
      userService,
      utilService
    ) {
      const { inEComLightMode } = eComLightService
      const setTotalRewards = () => {
        $scope.rewards = loyaltyService.getVisibleRewards(
          cartService.cart?.cart_totals?.[cartService.getCurrentCartType()]?.reward_discounts
        )
      }

      Object.assign(
        $scope,
        {
          $state,
          auth,
          cartService,
          configurableProductService,
          currencyFilter: $filter('currency'),
          express,
          EVENTS,
          IC_PLACEMENTS,
          imageURLService,
          inEComLightMode,
          listPrintService,
          reservationService,
          shoppingSelectorService,
        },
        {
          getMinCartSpend: shoppingListService.commonUtils.getMinimumCartSpend,
          shoppingIntent: auth.getContext().intent,
          showNotesSubs: false,
          hasCartItems: () =>
            cartService
              .getCartTypeToItemsMap()
              [cartService.getCurrentCartType()]?.filter(
                item => item?.store_product?.product_type === 'product'
              )?.length > 0,
          isNewCheckout: config.newCheckoutFlow.cartReviewPage.enabled,
          showLoyaltyCartPagePlacement: () =>
            loyaltyService.showLoyaltyPlacement() && config.loyalty.enableLoyaltyOptIn.cartPage,
          showLoyaltyCardBalance: () =>
            userService.loyaltyCards.length > 0 &&
            // eslint-disable-next-line no-restricted-globals
            !isNaN(userService.getLoyaltyCard().points_balance) &&
            config.newCheckoutFlow.cartReviewPage.enabled &&
            mobileService.isMobile &&
            config.showLoyaltyBalanceInCart &&
            config.hasLoyalty,
          loyaltyReactProps: {
            ...reactCartLoyaltyBalanceDefaultProps,
            styles: { value: { color: tokens.color.brand.primary.dark } },
          },
        }
      )

      let initialCartItems = []
      let initialCartComment = ''

      const copyCart = cart => {
        initialCartItems = angular.copy(cart.items)
        initialCartComment = cart.comment
      }

      if (!$scope.cartService.cart) {
        cartService.fetch()
      }

      $scope.outOfStockWarningPopover = $sce.trustAsHtml(
        `<span class="icon-warning add-to-cart__warning" aria-hidden="true"></span> ${lang.values.outOfStockDescription}`
      )

      $scope.handleDeleteConfirmCancelClick = item => {
        item.quantity = item.quantity || 1
        item.showDeleteConfirm = false
        // a11y fix to refocus on remove element after cancelling
        const itemID = `checkout-cart-item-${item.store_product.id}-remove`
        const removeButtonForItem = document.getElementById(itemID)
        if (removeButtonForItem) {
          setTimeout(() => {
            removeButtonForItem.focus()
          }, 0)
        }
      }

      $scope.handleExpressPlacementClick = () => {
        express.acknowledgePlacement(IC_PLACEMENTS.SIDECART)
        $state.go('main.icExpress')
      }

      $scope.emptyCartMessage = lang.template(lang.values.noItemsInCart, {
        shoppingMode: auth.getCurrentShoppingMode(),
      })

      $scope.breadcrumbs = navService.getCartListReviewItems()

      $scope.shouldShowCartUpsell = () =>
        !$scope.inEComLightMode &&
        cartService.cart &&
        cartService.cart.items.length > 0 &&
        config.cartUpsellCollection &&
        $scope.upsellItems &&
        $scope.upsellItems.length &&
        !$scope.loading

      $scope.$watch('cartService.isSubstitutionNotesOpen', isOpen => {
        $scope.showNotesSubs = isOpen

        if (isOpen) {
          copyCart($scope.cartService.cart)
        }
      })

      $scope.inCatering = auth.getCurrentShoppingMode() === SHOPPING_MODES.CATERING

      const handleSubistitutionNotesDiscard = () => {
        const { cart } = $scope.cartService

        cart.items = initialCartItems.map(item => {
          cartService.currentEnterpriseService.actions.updateItemComment(item, item.comment)
          return item
        })

        cart.comment = initialCartComment

        $scope.cartService.sync().then($scope.categorizeProducts)
      }

      if (mobileService.isMobile) {
        $scope.$on('mobileDiscardSubsNotes', handleSubistitutionNotesDiscard)
      }

      // Refresh cart on each view load
      $scope.loading = true

      // Prepare the cart
      const initProducts = function () {
        if ($scope.cartService.cart && $scope.cartService.cart.items.length) {
          _.each($scope.cartService.cart.items, item => {
            if (item.store_product) {
              item.isSelected = false
            }
          })

          // Initialize substitution toggles

          $scope.setAllowAllSubstitutionsToggle()

          // Generate initial list of categorized products and
          // update when cart changes

          $scope.categorizeProducts()
          if (!config.disableAisles && $scope.shoppingIntent === 'instore') {
            $scope.sortProductsByAisle()
          }
        }

        $scope.$watch('cartService.lastUpdated', () => {
          $scope.categorizeProducts()

          if (!config.disableAisles && $scope.shoppingIntent === 'instore') {
            $scope.sortProductsByAisle()
          }
        })

        // Stop loading
        $scope.loading = false
      }

      // Store sorted products by sortType
      $scope.sortedProductsBy = {}

      // Store sorted sortTypes (categories, aisles, etc.)
      $scope.orderedGroups = {}

      // Cart items grouped by their store
      $scope.dataByCartType = {}

      // Sort products by category
      $scope.categorizedProducts = {}

      $scope.categorizeProducts = function () {
        cartService
          .getCategorizedProductsAndCategories()
          .then(({ productsByCategory, orderedCategories }) => {
            $scope.categorizedProducts = productsByCategory
            $scope.orderedCategories = orderedCategories
            $scope.sortedProductsBy.category = $scope.categorizedProducts
            $scope.orderedGroups.category = $scope.orderedCategories
            cartService.createCartTypeToCategoryMapping()

            // This is for multi context side carts
            $scope.dataByCartType = $scope.getDataByCartType(cartService.cart)
          })
      }

      $scope.getDataByCartType = cart =>
        $scope.useMultiContextCart &&
        getDataByCartType(cart?.items, cart?.cart_totals, config.multipleBanners.banners)

      // Sort products by Aisle

      $scope.productsSortedByAisle = {}

      $scope.sortProductsByAisle = function () {
        $scope.sortedProductsBy.aisle = $scope.cartService.getAllProductsByAisle()
        $scope.orderedGroups.aisle = _.keys($scope.sortedProductsBy.aisle).sort((a, b) =>
          a.localeCompare(b)
        )
      }

      // Substitution toggling logic
      $scope.toggleAllowSubstitutions = item => {
        cartService.currentEnterpriseService.actions.updateItemAllowSubstitutions(
          item,
          item.allow_substitutions
        )
        $scope.cartService.sync(false)
        $scope.setAllowAllSubstitutionsToggle()
      }

      $scope.toggleAllowAllSubstitutions = function (status) {
        $scope.allowAllSubstitutions = !$scope.allowAllSubstitutions
        if ($scope.cartService.cart) {
          _.each($scope.cartService.cart.items, item => {
            if (!$scope.isAllowSubstitutionsDisabled(item)) {
              cartService.currentEnterpriseService.actions.updateItemAllowSubstitutions(
                item,
                status || $scope.allowAllSubstitutions
              )
            }
          })

          $scope.cartService.sync()
        }
      }

      $scope.setAllowAllSubstitutionsToggle = function () {
        $scope.allowAllSubstitutions = _.every($scope.cartService.cart.items, {
          allow_substitutions: true,
        })
      }

      $scope.isAllowSubstitutionsDisabled = function (item) {
        return !$scope.config.enableAllowSubstitutionsForConfigurableItems && item.product_config
      }

      $scope.substitutionCommentUpdate = item => {
        cartService.currentEnterpriseService.actions.updateItemComment(item, item.comment)
        cartService.sync()
      }

      // Handle promo codes

      $scope.promo = {}
      $scope.promo_ = {}
      $scope.addPromo = function (promo) {
        if (promo.promo_code && promo.promo_code.length) {
          promoCodeValidationService
            .checkIfValidPromoCode(promo)
            .then(result => {
              if (result.offer_id) {
                offersService.getOfferData(result.offer_id).then(offer => {
                  cartService.cart.promo_codes.push(promo.promo_code)
                  checkoutAnalytics.addPromo({ promoCode: promo.promo_code, offer, success: true })
                  delete promo.promo_code
                  cartService.sync()
                })
              }
            })
            .catch(err => {
              checkoutAnalytics.addPromo({ promoCode: promo.promo_code, success: false })
              errorModalService.open(err.data?.message || err.message, 'OK')
            })
        }
      }

      // Cart Upsell
      if (config.cartUpsellCollection && $scope.loadCartUpsell) {
        Restangular.all(config.cartUpsellCollection)
          .getList({ limit: 4 })
          .then(response => ($scope.upsellItems = response.plain()))
      }

      // Prepare the cart
      initProducts()

      $scope.$on('finishedEditingNotes', () => {
        $scope.showNotesSubs = !$scope.showNotesSubs
        cartService.isSubstitutionNotesOpen = $scope.showNotesSubs
      })

      $scope.updateItemComment = item => {
        cartService.currentEnterpriseService.actions.updateItemComment(item, item.comment)
        cartService.sync()
      }

      $scope.getBanner = warehouseId =>
        config.multipleBanners.banners.find(banner => banner.bannerExtId === warehouseId)

      $scope.goToProductDetailsModal = item => {
        // do not pop modal for config product ever
        if (item.product_config) return

        if (item?.store_id !== parseInt(auth.getStore(), 10)) {
          const currentWarehouse = $scope.auth.user.store.store_banner.name
          const productBanner = $scope.getBanner(item.warehouse_id)
          const productWarehouse = productBanner?.name
          const strings = {
            title: lang.template(lang.values.cantOpenPdpTitle, {
              productType: _.upperFirst(item.cart_type),
              currentWarehouse,
            }),
            message: lang.template(lang.values.cantOpenPdpMessage, { productWarehouse }),
            confirm: lang.template(lang.values.cantOpenPdpSwitchTo, { productWarehouse }),
            cancel: lang.template(lang.values.cantOpenPdpStayIn, { currentWarehouse }),
          }
          confirmationModal.open(strings, unableToOpenPDP).then(proceed => {
            if (proceed) {
              auth.update({ store_id: item.store_id, has_changed_store: true }).then(() =>
                auth
                  .setContext({
                    intent: auth.getContext().intent,
                    storeNumber: item.store_id,
                    retailer_slug: productBanner?.bannerSlug,
                  })
                  .then(() =>
                    // Available categories may change, so refresh them.
                    catalogCategoriesService.fetchCategories(true).then(() => {
                      $state.go('main.product', { id: item.store_product.id })
                    })
                  )
                  .then(() => auth.sendUserUpdatedStore(auth.user))
              )
            }
          })
        } else if (!item.product_config) {
          productDetailsModal.open(item.store_product.id, {
            location: config.analyticsSourceKeys.checkout,
          })
        }
      }

      // We have to use the upsell products from the service as it is the truth
      const showManualUpsell = () =>
        ((config.enableCheckoutManualUpsell && manualUpsellService?.manualUpsellProducts?.length) ||
          0) > 0

      const getUpsellRoute = () => {
        return config.newCheckoutFlow.upsellPage.enabled
          ? 'main.checkout.customUpsell'
          : 'main.checkout.manualUpsell'
      }

      const getCheckoutRoute = () => {
        if (config.newCheckoutFlow.checkoutPage.enabled) {
          return 'main.checkout.checkoutPage'
        }
        if (auth.shouldUseGigyaAddressEdit()) {
          return 'main.checkout.reservation.confirmation'
        }

        return 'main.checkout.reservation.address'
      }

      const proceedToNextCheckoutStep = () => {
        const url =
          showManualUpsell() && cartService.getCurrentCartType() !== CART_TYPES.CATERING
            ? getUpsellRoute()
            : getCheckoutRoute()

        // TODO: this is a workaround so a clients android app does not use the mobileNativeHrefService which is broken
        // This only seems to be an issue on Android for one client on checkout v1
        if (
          mobileService.isMobileApp() &&
          config.checkout.openNativeInWebview &&
          config.checkout.openNativeInWebviewOnlyiOS &&
          !mobileService.isIOS()
        ) {
          $state.go(url)
          return
        }

        if (mobileService.isMobileApp() && config.checkout.openNativeInWebview) {
          mobileNativeHrefService.openCheckoutInInappBrowser(
            $state.href(url, {}, { inherit: false })
          )
          return
        }

        $state.go(url)
      }

      const getClickAnalyticsPayload = cart => {
        if (!cart) return {}

        return {
          cart: {
            cart_items_count: cart.items.length,
            cart_total: cart.total,
            credit_total: cart.credit_total,
            delivery_fee: cart.delivery_fee,
            offer_total: cart.offer_total,
            pickup_fee: cart.pickup_fee,
            pre_discount_product_total: cart.pre_discount_product_total,
            product_total: cart.product_total,
            subscription_total: cart.subscription_total,
            tax_total: cart.tax_total,
            tip_percent: cart.tip_percent,
            tip_total: cart.tip_total,
            order_id: cart?.order?.id,
          },
          cart_products: cart.items?.map(i => i?.id),
        }
      }

      /**
       * Method that gets executed after the user clicks in the checkout button
       *
       * @param {string} - analyticsValue
       * @returns {undefined | boolean}
       */
      $scope.checkoutCartContinue = analyticsValue => {
        const proceedToCheckout = () => {
          const cart = cartService.cart

          analytics.logBeginCheckout(cart)
          analytics.logCheckoutCTAClick(analyticsValue, getClickAnalyticsPayload(cart))
          proceedToNextCheckoutStep()
        }

        const continueCallback = () => {
          checkoutAnalytics.checkoutClick()

          // Check if there is a coupon that needs to be clipped before proceeding to the next step.
          if (isThereUnclippedCouponWithProgressComplete()) {
            couponUnclippedModalService
              .open()
              // Ok Button action => Go to offers part of the page.
              .then(() => utilService.scrollIntoView(document.querySelector('#cart-offers')))
              // Cancel Button action
              .catch(() => proceedToCheckout())
          } else {
            // No unclipped coupon
            proceedToCheckout()
          }
        }

        if (config.alcoholCompliance.enabled && cartService.getHasAlcohol()) {
          if (
            $rootScope.modifyingOrder &&
            cartService.cart.order?.leave_unattended &&
            auth.getContext().intent === SHOPPING_MODES.DELIVERY
          ) {
            errorModalService.openCustom(alcoholValidationErrorModalTemplate, {
              lang,
              isAlcoholError: true,
            })
            return false
          }

          alcoholValidation.open().then(continueCallback)
          return false
        }

        continueCallback()
      }

      // Check to see if there exists at least one coupon that is unclipped with complete progress.

      function isThereUnclippedCouponWithProgressComplete() {
        let isUnclippedCouponProgressComplete = false
        _.forEach(cartService.cart.offer_progress, offer => {
          if (offer.offer.clipped === false && offer.completion_count > 0) {
            isUnclippedCouponProgressComplete = true
            // TODO What does this return do?
          }
        })

        return isUnclippedCouponProgressComplete
      }

      // Used to ensure ng-repeat's `track by` works with one-time binding
      $scope.generateUniqueItemId = item =>
        `${item.store_product.id}-${configurableProductService.generateConfigIdString(item)}`

      /**
       * Handles the increment item button click
       * @param {object} item
       *
       * @returns {undefined}
       */
      $scope.incrementItem = item => {
        cartService.incrementItem(item.store_product, 1, item.product_config)
        checkoutAnalytics.cartEditQuantity({ success: true })
      }

      /**
       * Handles the decrement item button click
       * @param {object} item
       *
       * @returns {undefined}
       */
      $scope.decrementItem = item => {
        if (item.quantity > 1) {
          cartService.decrementItem(item.store_product, 1, item.product_config)
        } else {
          item.showDeleteConfirm = true
        }

        checkoutAnalytics.cartEditQuantity({ success: true })
      }

      /**
       * Triggered when the weight is updated
       *
       * @param {object} item                - The product item being updated
       * @param {number} $originalQuantity   - [Provided] The original quantity before the change
       * @param {function} $onZeroFn         - [Provided] Callback to trigger if the new quantity is zero
       *
       * @returns {undefined}
       */
      $scope.updateItemQuantity = (item, $originalQuantity, $onZeroFn) => {
        const isZero = item.quantity === 0
        if ($onZeroFn && isZero) {
          $onZeroFn()
          item.quantity = $originalQuantity
          return
        }

        const { store_product, quantity, product_config } = item
        cartService.updateItemQuantity(store_product, quantity, product_config?.options)
      }

      /**
       * Opens the add promo code modal
       *
       * @returns {undefined}
       */
      $scope.openPromoCodeModal = () => {
        const onClose = () => {
          cartService.sync()
          const promoLink = document.getElementsByClassName('mobile-promo-codes__link')
          if (promoLink.length > 0) {
            promoLink[0].focus()
          }
        }
        utilService.openPromoCodeModal(onClose, mobileService.isMobile)
      }

      $scope.modifyNote = () => {
        analytics.logEvent('cart save comment update', 'update')
        const note = cartService.cart?.comment
        cartService
          .sync()
          .then(() => checkoutAnalytics.modifyNote({ success: true, note }))
          .catch(() => checkoutAnalytics.modifyNote({ success: false, note }))
      }

      $scope.isItemCurrentlyOutOfStock = item => {
        return !enterpriseProductService.utils.isProductAvailable(item.store_product)
      }

      if (config.rewards.visible.length) {
        $scope.$watch(() => cartService.syncing, setTotalRewards)
        setTotalRewards()
      }
    }
  )
  .run($templateCache => {
    $templateCache.put('checkoutCartTable.html', require('./views/checkoutCartTable.html'))
    $templateCache.put('reservation.html', require('../reservation/views/reservation.html'))
  })
