import angular from 'angular'
import template from './cartListTransferState.html'
import CartListTransferStateControllerModule from './cartListTransferState.controller.js'
import CartListTransferComponentModule from './cartListTransfer.component.js'

export default angular
  .module('CartListTransferModule', [
    CartListTransferStateControllerModule.name,
    CartListTransferComponentModule.name,
  ])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('cartListTransfer', {
      url: '/cart_transfer?initialContext&updatedContext&redirect',
      views: {
        'content@': {
          template,
          controller: 'CartListTransferStateController',
        },
      },
      resolve: {
        title(titleService) {
          return titleService.setTitle(langProvider.values.cartListTransfer)
        },
      },
      onEnter: ($stateParams, auth, config) => {
        if (!auth.user.context || !auth.user.context.intent) {
          auth.setContext({ intent: config.defaultShoppingContext })
        }
      },
    })
  })
