import angular from 'angular'

export default angular.module('ListsComponentModule', []).config(($stateProvider, langProvider) => {
  $stateProvider
    .state('lists', {
      parent: 'main.account',
      url: '/lists',
      views: {
        'content@main.account': {
          template: require('./lists.html'),
          controller: 'ListsController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "listsController", webpackPrefetch: true */ './listsController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.saveForLaterLists)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.saveForLaterLists,
          },
        ],
      },
    })

    .state('list', {
      parent: 'lists',
      url: '/:list_id',
      views: {
        'content@main.account': {
          template: require('./list.html'),
          controller: 'ListController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "listController", webpackPrefetch: true */ './listController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        listDetails(list, $stateParams) {
          return list
            .getList($stateParams.list_id, false)
            .then(response => angular.copy(response.payload))
            .catch(_ => {
              // Getting the list details involves an HTTP request whose errors are caught by
              // Restangular.setErrorInterceptor(...), which shows an error modal. And, if we return
              // a rejected promise with a payload containing a `500 - Internal Server Error` error,
              // the $stateChangeError listener in auth.js will also show an error modal.
              //
              // To work around this, we ignore the error from getList and return a custom error
              // instead.
              //
              // ENT-35746
              throw new Error(
                `Unexpected error retrieving list details for list ${$stateParams.list_id}`
              )
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.saveForLaterListDetails)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            state: 'lists',
            label: langProvider.values.saveForLaterLists,
          },
          {
            label: langProvider.values.saveForLaterListDetails,
          },
        ],
      },
    })
})
