import angular from 'angular'

export default angular
  .module('ICExpressPostCheckoutCreditCardPageControllerModule', [])
  .controller(
    'IcExpressPostCheckoutCreditCardPageController',
    function ($filter, $scope, $state, config, express, lang, mobileService, order) {
      Object.assign($scope, {
        lang,
        order,
        config,
        $filter,
        express,
        mobileService,
        goToOrderPage: () => $state.go('main.checkout.confirmation', { orderId: order.id }),
      })
    }
  )
