import angular from 'angular'

export default angular
  .module('orderFeesModule', [])
  .service('orderFeesService', function orderFeesService(cartService, config, lang, utilService) {
    const service = this

    service.feeConfigs = angular.copy(config.additionalFees)
    service.feeConfigs.forEach(fee => {
      fee.sourceKeys = fee.key
      fee.key = fee.key.join(',')
    })

    /**
     * Returns a list of formatted fees for the totals line items component
     * @param {Array<Object | Function>} sources - An array of objects or function
     * @param {string} shoppingIntent            - The shopping intent
     * @return {[<Object>]}                      - An array of formatted fee objects
     */
    service.formattedFees = (sources, shoppingIntent) =>
      service.feesWithValues(sources).reduce((formattedFees, fee) => {
        if (!service.isSubfee(fee) && service.shouldShowFee(fee)) {
          const feeValue = parseFloat(fee.value)
          const originalFeeValue = parseFloat(fee.originalFeeValue)
          const subFees = service
            .getSubfees(fee)
            .filter(subFee => service.shouldShowFee(subFee))
            .map(subFee => ({
              label: service.getLabel(subFee, shoppingIntent),
              value: parseFloat(subFee.value),
              description: lang.values[subFee.descriptionReference],
            }))

          return [
            ...formattedFees,
            {
              type: 'value',
              label: service.getLabel(fee, shoppingIntent),
              value: feeValue,
              tooltip: fee.tooltipReference && lang.values[fee.tooltipReference],
              previousValue:
                originalFeeValue > 0 && originalFeeValue > fee.value ? originalFeeValue : undefined,
              showFreeIfZero: true,
              subItems: subFees,
            },
          ]
        }

        return formattedFees
      }, [])

    /**
     * Returns a list of showable fees
     * @param {Array<Object | Function>} sources - An array of objects or function
     * @return {[<Object>]}                      - An array of fee objects
     */
    service.feesWithValues = sources => {
      const { feeConfigs } = service
      const serverFees = reduceServerFees(sources)

      feeConfigs.forEach(fee => {
        fee.value = getValue(fee, serverFees)

        const originalFeeKey = fee.meta?.originalFeeKey

        // Add "original fee" (fee before free or discount) if it exists
        if (originalFeeKey && serverFees[originalFeeKey]) {
          fee.originalFeeValue = serverFees[originalFeeKey]
        }
      })

      return feeConfigs.filter(fee => fee.key !== 'pickup_fee' || !config.disablePickFees)
    }

    /**
     * Checks if the fee is a subfee for some other fee
     *
     * @param {Object} fee     - A fee config object
     * @return {Boolean}    - True if part of another fee
     */
    service.isSubfee = fee => {
      const { feeConfigs } = service

      return !!feeConfigs.find(parentFee => parentFee.subfees?.includes(fee.key))
    }

    /**
     * Gets the list of subfees as fee config objects
     *
     * @param {Object} fee     - A fee config object
     * @return {[Object]}    - An array of fee objects
     */
    service.getSubfees = fee => {
      if (!fee.subfees) return []
      return service.feeConfigs.filter(subfee => fee.subfees.includes(subfee.key))
    }

    /**
     * Generate a custom label if the labelFn was provided
     *
     * @param {Object} fee             - A fee config object
     * @param {String} shoppingIntent  - The shopping intent
     * @return {String}                - The custom label (or the default if none returned).
     */
    service.getLabel = (fee, shoppingIntent) => {
      const { labelFn } = service
      const { labelReference, labelReferenceOverrides } = fee

      const isIcFulfilled = config.shoppingSelector[shoppingIntent]?.fulfilledByInstacart
      const labelRefKey = `${isIcFulfilled ? 'ic:' : ''}${shoppingIntent}`
      const labelRef = labelReferenceOverrides?.[labelRefKey] || labelReference

      return (labelFn && labelFn(fee)) || lang.values[labelRef] || fee.key
    }

    /**
     * Whether fee is free or not
     *
     * @param {Object} fee      - A fee config object
     * @return {Boolean}        - True if free
     */
    service.isFree = fee => {
      const { whenZero } = fee.free || {}
      return whenZero && utilService.isZero(fee)
    }

    /**
     * Determines whether the a fee is waived by the selected subscription
     *
     * @param {Object} fee      - A fee config object
     * @return {Boolean}        - True if waived by subscription
     */
    service.doesSubscriptionWaiveFee = fee => {
      return service.cartService?.selectedSubscription?.waiving_fee_types.includes(fee.key)
    }

    /**
     * Whether or not the fee should be displayed
     *
     * @param {Object} fee      - A fee config object
     * @return {Boolean}        - True if should display
     */
    service.shouldShowFee = fee => {
      return (
        fee.value !== null &&
        (utilService.isNonZero(fee) || service.isFree(fee) || fee.originalFeeValue)
      )
    }
  })

/**
 * Takes in array of source objects, or functions returning objects, and spits out one single object with a summary of
 * different fees.
 *
 * @param {Array<Object | Function>} sources      - An array of objects or function
 *
 * @return {Object}                               - A single object with source keys mapped to values
 *
 * @example (sourceKeys = ['pick_fee', 'other_fee'])
 *  IN:  [ () => { pick_fee: 2 }, { etc: true }, { pick_fee: 1, other_fee: 3 }]
 *  OUT: { pick_fee: 2, other_fee: 3 }
 *
 * @future: The sources should be cached, if there's no change, return the previous result
 */
function reduceServerFees(sources) {
  if (!sources) return {}

  // This works by doing the following:
  //    0. If a source is a function, execute to get the source
  //    1. Get the entry set (array) for each source object
  //    2. Implicitly flatten the entry sets (up to 1 depth) to one array
  //    3. Implicitly reverse the array
  //    4. Reduce the array to an object, taking note of keys if defined
  return sources
    .map(source => (typeof source === 'function' ? source() : source))
    .flatMap(source => Object.entries(source || {}))
    .reduceRight((map, [key, val]) => Object.assign(map, { [key]: val }), {})
}

/**
 * The value of a fee
 *
 * @param {Object} fee        - A fee config object as returned by the array of fee configs
 * @param {Object} feeSource  - The source of reduced fees as returned by the server
 *
 * @return {Float | null}  - A fee value, or null if not found
 */
function getValue(fee, feeSource) {
  const mapKeyToVal = key => {
    // If its a composed fee, calculate the value based on the subfees if they are present
    fee.isComposed = fee.subfees?.length && fee.subfees.every(subfee => subfee in feeSource)

    if (fee.isComposed)
      return fee.subfees.reduce((total, subkey) => total + Number(feeSource[subkey]), 0)

    return feeSource[key]
  }

  const value = fee.sourceKeys
    .map(mapKeyToVal)
    .filter(x => x !== undefined && x !== null)
    .sort()
    .pop()

  return value !== undefined ? value : null
}
