/* eslint-disable complexity */
import React, { memo, forwardRef, useState, useMemo } from 'react'

import PropTypes from 'prop-types'

import {
  Button,
  createStyles,
  Text,
  utils,
  GridContainer,
  GridItem,
  Alert,
  LoadingDots,
  useMediaQuery,
} from '@instacart/cocktail'

import { FULFILLMENT_TYPES } from '@app/angular/constants/checkout'
import { getMainLabel } from '../utils'
import { TIMESLOT_FEES, TIMESLOT_POLYTYPES } from '../constants'

const isHideTimeslotFee = (timeslot, showTimeslotPickupFeeAsFree) =>
  timeslot.fulfillment_type === FULFILLMENT_TYPES.PICKUP && !showTimeslotPickupFeeAsFree

const useStyles = props =>
  createStyles(tokens => ({
    root: {
      color: tokens.color.text.primary,
      borderRadius: tokens.border.radius.small,
      width: '100%',
      border: utils.toPx`${tokens.border.width.regular} solid ${tokens.color.border.light}`,
      backgroundColor: tokens.color.neutral.white,
      textAlign: 'center',
      margin: tokens.size.spacing.none,
      '&:hover': {
        backgroundColor: tokens.color.neutral.grey1,
        cursor: 'pointer',
      },
      [utils.mediaQuery.maxWidth(tokens.breakpoint.small)]: {
        border: 'none',
        borderBottom: utils.toPx`${tokens.border.width.regular} solid ${tokens.color.border.light}`,
      },
    },
    buttonSelected: {
      border: utils.toPx`${tokens.border.width.regular} solid ${tokens.color.brand.primary.regular}`,
    },
    timeRangeText: {
      fontWeight: tokens.font.weight.bold,
    },
    selectButton: {
      pointerEvents: 'none',
      minWidth: 90,
    },
    readOnly: {
      '&:hover': {
        backgroundColor: tokens.color.neutral.white,
        cursor: 'default',
      },
    },
    error: {
      paddingTop: 0,
      textAlign: 'initial',
    },
    timeLabel: {
      textAlign: 'left',
      paddingLeft: isHideTimeslotFee(props.timeSlot.timeslot, props.showTimeslotPickupFeeAsFree)
        ? 0
        : tokens.size.spacing.xxLarge,
      [utils.mediaQuery.maxWidth(tokens.breakpoint.small)]: {
        paddingLeft: 0,
      },
    },
    unavailableText: {
      color: tokens.color.ui.error,
    },
    unavailable: {
      color: tokens.color.text.secondary,
    },
    unavailableReason: {
      fontFamily: tokens.font.family.primary,
      color: tokens.color.text.secondary,
      paddingLeft: tokens.size.spacing.xxLarge,
      textAlign: 'left',
    },
    showDetailButtonOverrides: {
      border: 'none',
      backgroundColor: 'transparent',
      padding: 0,
      fontFamily: tokens.font.family.primary,
      color: tokens.color.text.secondary,
      '&:hover': {
        cursor: 'pointer',
      },
    },
  }))(props)

export const defaultLang = {
  selected: 'Selected',
  select: 'Select',
  noAlcohol: 'No Alcohol',
  unavailable: 'Unavailable',
  hideDetail: 'Hide detail',
  showDetail: 'Show detail',
  withinHours: 'Within {{hours}} hour{{plural}}',
  free: 'Free',
  highDemand: 'Demand is high, your order will be taken by the first available shopper.',
  blackoutUnavailableReason: 'There are items in your cart that are unavailable during this time.',
}

const TimeSlot = forwardRef((props, ref) => {
  const styles = useStyles(props)
  const {
    timeSlot,
    isSelected,
    i18n,
    onSelected,
    readOnly,
    error,
    isAttemptingToSelect,
    showTimeslotPickupFeeAsFree = true,
    ...restProps
  } = props

  const lang = useMemo(() => ({ ...defaultLang, ...i18n }), [i18n])
  const slotIsUnavailable = !!(
    timeSlot.timeslot?.unavailable ||
    timeSlot.timeslot?.unavailability_reasons?.length ||
    timeSlot.timeslot?.during_blackout
  )
  const handleSelection = () => {
    if (!readOnly && !error && !slotIsUnavailable) {
      onSelected(timeSlot)
    }
  }
  const onKeyDown = event => {
    if (event.key === 'Enter' || event.key === 'Spacebar' || event.key === ' ') {
      handleSelection()
    }
  }
  const [showUnavailableDetail, setShowUnavailableDetail] = useState(false)
  const isMobile = useMediaQuery(tokens => utils.mediaQuery.maxWidth(tokens.breakpoint.small))
  const hideFreeText = isHideTimeslotFee(timeSlot.timeslot, showTimeslotPickupFeeAsFree)

  const formatFeePrice = ({ timeslot }) => {
    const shoppingModeFee =
      timeslot[TIMESLOT_FEES[timeslot.source][timeslot.fulfillment_type] || TIMESLOT_FEES.DEFAULT]

    const freeText = hideFreeText ? '' : lang.free

    return timeslot.is_free || parseFloat(shoppingModeFee) === 0
      ? freeText
      : utils.formatCurrency(shoppingModeFee)
  }

  const pickupFee = (
    <span>
      <Text component="span" variant="body" css={styles.fee} data-test="time-slot-fee">
        {formatFeePrice(timeSlot)}
      </Text>
    </span>
  )

  const handleUnavailableClick = e => {
    e.stopPropagation()
    setShowUnavailableDetail(!showUnavailableDetail)
  }

  const unavailabilityReasons = useMemo(
    () =>
      [
        ...(timeSlot.timeslot?.unavailability_reasons || []),
        timeSlot.timeslot?.during_blackout ? lang.blackoutUnavailableReason : null,
      ].filter(reason => reason),
    [timeSlot, lang]
  )

  return (
    <GridContainer
      ref={ref}
      css={[
        styles.root,
        isSelected ? styles.buttonSelected : {},
        readOnly || error ? styles.readOnly : {},
      ]}
      justify="space-evenly"
      align="center"
      onClick={() => handleSelection()}
      onKeyDown={onKeyDown}
      {...restProps}
    >
      <GridItem
        xSmall={readOnly && !slotIsUnavailable ? 12 : 8}
        small={slotIsUnavailable || readOnly ? 8 : 5}
      >
        <div css={[styles.timeLabel, slotIsUnavailable ? styles.unavailable : {}]}>
          <Text bold data-test="time-slot-main-label">
            {getMainLabel(timeSlot, { showDate: false })}
          </Text>
          {/* We need to treat null as true here, as BE will return null for some retailers */}
          {!timeSlot.timeslot.allow_alcohol && timeSlot.timeslot.allow_alcohol !== null && (
            <Text variant="caption" data-test="time-slot-no-alcohol-label">
              {lang.noAlcohol}
            </Text>
          )}
          {timeSlot.timeslot.polytype === TIMESLOT_POLYTYPES.ASAP && (
            <Text data-test="time-slot-high-demand-label" variant="caption">
              {lang.highDemand}
            </Text>
          )}
          {!!timeSlot.timeslot.during_blackout && (
            <Text data-test="time-slot-high-demand-label" variant="caption">
              {lang.unavailable}
            </Text>
          )}

          {slotIsUnavailable ? (
            <button
              css={styles.showDetailButtonOverrides}
              data-test="show-hide-detail"
              onClick={handleUnavailableClick}
            >
              <Text>{showUnavailableDetail ? lang.hideDetail : lang.showDetail}</Text>
            </button>
          ) : (
            <></>
          )}
          {!slotIsUnavailable && isMobile ? pickupFee : <></>}
        </div>
      </GridItem>

      {!isMobile && !slotIsUnavailable ? <GridItem small={3}>{pickupFee}</GridItem> : <></>}

      {!slotIsUnavailable && !readOnly ? (
        <GridItem xSmall={4}>
          <Button
            aria-current={isSelected}
            variant={isSelected ? 'filled' : 'outline'}
            css={styles.selectButton}
            data-test="time-slot-select-button"
            disabled={error}
          >
            {isAttemptingToSelect && !error ? (
              <LoadingDots />
            ) : isSelected ? (
              lang.selected
            ) : (
              lang.select
            )}
          </Button>
        </GridItem>
      ) : slotIsUnavailable ? (
        <GridItem xSmall={4}>
          <Text data-test="time-slot-unavailable" css={styles.unavailableText}>
            {lang.unavailable}
          </Text>
        </GridItem>
      ) : (
        <></>
      )}

      {error ? (
        <GridItem xSmall={12} css={styles.error} data-test="time-slot-error">
          <Alert variant="error">{error.message}</Alert>
        </GridItem>
      ) : (
        <></>
      )}
      {slotIsUnavailable && showUnavailableDetail && unavailabilityReasons.length ? (
        <GridItem xSmall={12} data-test="time-slot-unavailable-msg">
          {unavailabilityReasons.map(reason => (
            <Text align="left" variant="caption" key={reason} style={styles.unavailableReason}>
              {reason}
            </Text>
          ))}
        </GridItem>
      ) : (
        <></>
      )}
    </GridContainer>
  )
})

TimeSlot.propTypes = {
  /** The timeslot to display */
  // eslint-disable-next-line react/forbid-prop-types
  timeSlot: PropTypes.object,
  /** If the timeslot is selected */
  isSelected: PropTypes.bool,
  /** If the timeslot is attempting to be reserved */
  isAttemptingToSelect: PropTypes.bool,
  /** Custom lang object */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Function to call when a timeslot is selected */
  onSelected: PropTypes.func,
  /** Makes the component look like text and not a button */
  readOnly: PropTypes.bool,
  /** Decide when to display pickup timeslot as free */
  showTimeslotPickupFeeAsFree: PropTypes.bool,
  /** The error to display on the time slot */
  error: PropTypes.shape({
    message: PropTypes.string.isRequired,
  }),
}

export default memo(TimeSlot)
