import angular from 'angular'

export default angular
  .module('NavMenuCallbacksServicesModule', [])
  .service('navMenuCallbacksService', function navMenuCallbacksService() {
    const service = this

    service.callbacks = {}

    service.getCallbacks = () => service.callbacks

    service.setCallbacks = callbacks => {
      angular.copy(callbacks, service.callbacks)
    }
  })
