import angular from 'angular'
import { FULFILLMENT_TYPES } from '@app/angular/constants/checkout'
import {
  FIRST_ORDER_KEY,
  MEMBERSHIP_STATUSES,
  EXPRESS_SUBSCRIPTION_PLAN_TERMS,
} from '@app/angular/constants/express'
import { CART_TYPES } from '@app/angular/constants/cart'
import desktopTemplate from './views/checkoutPage.desktop.html'
import mobileTemplate from './views/checkoutPage.mobile.html'

export default angular
  .module('CheckoutPageControllerModule', [])
  .controller(
    'CheckoutPageController',
    function (
      $element,
      $filter,
      $q,
      $rootScope,
      $scope,
      $state,
      Restangular,
      addressList,
      analytics,
      auth,
      cartService,
      checkoutAnalytics,
      checkoutService,
      config,
      errorModalService,
      express,
      lang,
      localStorageService,
      mobileService,
      paymentGateway,
      reservationService,
      upsellProducts,
      utilService
    ) {
      const { intent } = auth
      const filterCurrencyFn = $filter('currency')

      Object.assign($scope, {
        CART_TYPES,
        cartService,
        checkoutAnalytics,
        config,
        /**
         * Needed for making the checkout-summary component aware of the
         * step that is currenly on edit mode
         */
        currentEditStep: null,
        gatewayService: null,
        intent,
        isPlaceOrderButtonEnabled: false,
        isPlacingOrder: false,
        lang,
        mobileService,
        payment: null,
        express,
        cartType: cartService.getCurrentCartType(),

        termsAndConditionsMessage: () => {
          const shoppingMode = auth.getCurrentShoppingMode()

          const {
            termsAndConditionsMessage,
            termsAndConditionsMessageDeferredAuth,
            termsAndConditionsByIntent,
          } = lang.values

          const message = reservationService?.reservation?.full_auth_will_defer
            ? termsAndConditionsMessageDeferredAuth
            : termsAndConditionsMessage

          return lang.template(termsAndConditionsByIntent?.[shoppingMode] || message, {
            customAuthAmount: filterCurrencyFn(cartService.getAuthorizationAmount()),
          })
        },
      })

      const focusOnPage = () => {
        const containerToFocusOn =
          document.getElementById('checkout-page-desktop-container') || $element[0]
        if (containerToFocusOn) {
          utilService.trapFocusOn(containerToFocusOn)
          containerToFocusOn.focus()
        }
      }

      const onInit = () => {
        focusOnPage()
      }
      onInit()

      /**
       * This method executes everytime the checkout-steps
       * component changes its validation status
       *
       * @returns { undefined }
       */
      $scope.handleValidationChange = isValid => {
        $scope.isPlaceOrderButtonEnabled = isValid
      }

      /**
       * Executes when a step changes to edit mode.
       *
       * @returns { undefined }
       */
      $scope.handleStepEditMode = stepID => {
        $scope.currentEditStep = stepID
      }

      const handleExpressSubscribeError = () => {
        const { subscriptionErrorMessage, name } = config.icExpress

        errorModalService
          .open(lang.template(subscriptionErrorMessage, { name }).toString(), lang.values.ok)
          .finally(focusOnPage())
        return $q.reject()
      }

      const handleExpressSubscribe = async () => {
        const membershipStatus = express.getMembershipStatus()

        if ($scope.shouldSubscribeToExpress() && !$scope.shouldSubscribeToExpressPostCheckout()) {
          const planId =
            membershipStatus === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE
              ? await express.getPlanIds(EXPRESS_SUBSCRIPTION_PLAN_TERMS.TRIAL)
              : await express.getPlanIds(EXPRESS_SUBSCRIPTION_PLAN_TERMS.YEARLY)

          const { ext_id, ext_payer_id } = $scope.payment?.auth_capture?.payment_instrument || {}

          const payload = {
            ext_id,
            ext_payer_id,
            subscription_plan_id: planId,
          }

          return express.subscribe(payload).then($q.resolve).catch(handleExpressSubscribeError)
        }

        return $q.resolve()
      }

      $scope.shouldSubscribeToExpress = () => {
        return (
          config.icExpress.enabled &&
          express.isIcExpressOptedIn &&
          [MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE, MEMBERSHIP_STATUSES.MEMBERSHIP_ELIGIBLE].includes(
            express.getMembershipStatus()
          )
        )
      }

      $scope.shouldSubscribeToExpressPostCheckout = () =>
        $scope.shouldSubscribeToExpress() && !express.shouldCreateSubscriptionAtCheckout()

      /**
       * Places an order
       *
       * @returns { Promise }
       */
      $scope.placeOrder = () => {
        $scope.isPlacingOrder = true

        return handleExpressSubscribe()
          .then($scope.handleBillingInformationCheckout)
          .then($scope.checkout)
          .finally(() => ($rootScope.isAppLoading = false))
      }

      const handleGenericError = () => {
        checkoutService.setError({ message: lang.values.defaultErrorMessage })
      }

      const handleBillingInformationError = () => {
        checkoutService.setError({ message: lang.values.invalidBillingAddress })
      }

      $scope.handleBillingInformationCheckout = () => {
        const isDelivery = auth.getContext().intent === FULFILLMENT_TYPES.DELIVERY
        const { billing_address } = auth.user

        if (!config.enableBillingInformation || $scope.payment.payment_type !== 'auth_capture') {
          return $q.resolve()
        }

        if (isDelivery && checkoutService.isBillingAddressSameAsDelivery) {
          return checkoutService
            .getSelectedDeliveryAddress(true)
            .then(({ address1, city, province, postal_code }) => {
              auth
                .updateProfile({
                  billing_address: {
                    address1,
                    city,
                    province,
                    postal_code,
                  },
                })
                .catch(handleGenericError)
            })
            .catch(handleGenericError)
        }
        if (!checkoutService.isBillingAddressValid(billing_address)) {
          handleBillingInformationError()
          return $q.reject()
        }
        return $q.resolve()
      }

      const _handleCheckoutSuccess = () => {
        const { icExpress } = config
        const { firstOrder } = icExpress.placements
        const shoppingIntent = auth.getContext().intent

        if (icExpress.enabled && firstOrder[shoppingIntent] && firstOrder[shoppingIntent].enabled) {
          localStorageService.deprecatedSet(FIRST_ORDER_KEY, true)
        }
        localStorageService.removeItem('firstIWSAlcoholicProductAdded')
        checkoutService.isCartValidated = false
      }

      /**
       * Handles checkout
       *
       * @returns {Promise}
       */
      $scope.checkout = () => {
        $rootScope.isAppLoading = true

        const additionalParams = {
          cart_id: cartService.getCartId(cartService.getCurrentCartType()),
          checkoutErrorCallback: focusOnPage,
        }

        if ($scope.shouldSubscribeToExpressPostCheckout()) {
          additionalParams.postCheckoutRoute = 'main.checkout.icExpressOrderConfirmation'
        }

        return checkoutService
          .performCheckout($scope.payment, $scope.gatewayService, additionalParams)
          .then(_handleCheckoutSuccess)
          .catch(() => {
            $scope.isPlacingOrder = false
            $rootScope.isAppLoading = false
          })
      }

      /**
       * Executes every time the payment method inside the checkout steps component
       * gets updated
       *
       * @returns {undefined}
       */
      $scope.handlePaymentUpdate = payment => {
        $scope.payment = payment
        if (payment?.payment_type) {
          $scope.gatewayService = paymentGateway.getService(payment.payment_type)
        }
      }

      /**
       * Executes every time the contact info inside the checkout steps component
       * gets updated
       *
       * @returns {undefined}
       */
      $scope.handleContactUpdate = contact => {
        $scope.contact = contact
      }

      const getUpsellRoute = () => {
        return config.newCheckoutFlow.upsellPage.enabled
          ? 'main.checkout.customUpsell'
          : 'main.checkout.manualUpsell'
      }
      /**
       * Handles going back from the checkout page
       *
       * @returns {undefined}
       */
      $scope.goBack = () => {
        $state.go(upsellProducts.length > 0 ? getUpsellRoute() : 'main.checkout.cart')
      }

      /**
       * Handles the checkout exit event by redirecting users to the cart review page
       *
       * @returns {undefined}
       */
      $scope.handleCheckoutExit = () => {
        $state.go('main.checkout.cart', { cart_type: $state.params.cart_type })
      }

      $scope.showTermsAndConditions = () => {
        const { rewards } = config
        const isRewardsEnabled = rewards.visible.length > 0
        const showTermsAndConditions =
          !cartService.isCartBelowMin() &&
          config.showTermsAndConditions[auth.getCurrentShoppingMode()]

        if (!isRewardsEnabled) return showTermsAndConditions

        return checkoutService.isCartValidated && showTermsAndConditions
      }
    }
  )
  .run($templateCache => {
    $templateCache.put('checkoutPage.desktop.html', desktopTemplate)
    $templateCache.put('checkoutPage.mobile.html', mobileTemplate)
  })
