import { isEmpty } from 'lodash'
import angular from 'angular'

export default angular.module('CartTotalsComponentModule', []).component('cartTotals', {
  bindings: {
    cssClass: '@',
    totals: '=cartTotals',
    showFees: '=',
    isOrder: '<',
  },
  controller($scope, cartService, config, lang, auth) {
    const vm = this

    vm.$onInit = () => {
      vm.isOrder = typeof vm.isOrder !== 'boolean' ? false : vm.isOrder
    }

    vm.getTaxTotal = () => {
      if (vm.isOrder) return vm.totals.tax_total

      return cartService.getFinalTaxTotal()
    }

    vm.getEstimatedTotal = () => {
      if (vm.isOrder) return vm.totals.total

      return cartService.getCartFinalTotal()
    }

    Object.assign(vm, { cartService, config, lang })

    vm.getTotalsNote = () => lang.values.intentTotalsNote[auth.getContext().intent]

    vm.isPickFeesVisible = () =>
      vm.showFees &&
      vm.totals.pickup_fee &&
      vm.totals.pickup_fee !== '0' &&
      isEmpty(vm.totals.additional_fees)

    vm.isSubscriptionFeesVisible = () =>
      vm.config.hasSubscriptions && isEmpty(vm.totals.additional_fees)

    vm.isDeliveryFeesVisible = () =>
      vm.showFees &&
      vm.totals.delivery_fee &&
      vm.totals.delivery_fee !== '0' &&
      isEmpty(vm.totals.additional_fees)
  },
  template: require('./cartTotals.html'),
})
