import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('cartListTransferServiceModule', [])
  .service(
    'cartListTransferService',
    function cartListTransferService($q, $state, $stateParams, Restangular, redirectService) {
      const service = this

      // eslint-disable-next-line eqeqeq
      const shouldRedirectToDotCom = $state.current.name == 'cartListTransfer'
      service.redirectToDotCom = () => {
        redirectService.goToRedirectUrl(
          { href: $stateParams && $stateParams.redirect },
          { state: 'cartListTransfer' }
        )
      }

      service.initializeTransfer = ({ initialContext, updatedContext, transferData }) => {
        const deferred = $q.defer()

        service.initialContext = initialContext
        service.updatedContext = updatedContext
        service.transferData = transferData
        service.transferStatus = { inProgress: false }

        // Callback methods for cartListTransferController to tell contextSelectorController.js if the user completed or cancelled the context switch
        service.completeContextSwitch = () => {
          service.transferStatus.inProgress = true
          deferred.resolve()

          if (shouldRedirectToDotCom) {
            service.redirectToDotCom()
          }
        }
        service.cancelContextSwitch = () => {
          deferred.reject()

          if (shouldRedirectToDotCom) {
            service.redirectToDotCom()
          }
        }

        return deferred.promise
      }

      service.transferItems = (redirectRoute = null) => {
        service.transferStatus.inProgress = true

        const transferEndpoints = {}

        if (service.updatedContext === 'instore') {
          transferEndpoints.from = 'cart'
          transferEndpoints.to = 'user_lists/default'
        } else {
          transferEndpoints.from = 'user_lists/default'
          transferEndpoints.to = 'cart'
        }

        const { transferableProducts } = service.transferData
        const clearTransferredProducts = _.map(transferableProducts, product =>
          _.assign({}, product, { quantity: 0 })
        )

        return $q
          .all([
            // Transfer cart/list items to the list/cart
            Restangular.one(transferEndpoints.to).customPUT({ items: transferableProducts }),
            // Remove items that we transferred to list/cart from the cart/list
            Restangular.one(transferEndpoints.from).customPUT({ items: clearTransferredProducts }),
          ])
          .then(() => redirectRoute && $state.go(redirectRoute))
          .then(() => {
            service.completeContextSwitch()
          })
      }
    }
  )
