import _ from 'lodash'
import angular from 'angular'
import { ADDRESS_FIELDS } from '@app/angular/constants/address'

import template from './addressListForm.html'

export default angular.module('AddressListFormComponentModule', []).component('addressListForm', {
  bindings: {
    data: '<',
    onSave: '&',
    onCancel: '&',
    onDelete: '&',
    validateAddress: '<',
    useContinueCopy: '<',
    saveToDb: '<',
    optionalFields: '<?',
  },
  template,
  controller($q, $scope, $state, $timeout, addressList, config, lang, registrationService) {
    const vm = this
    vm.addressList = addressList
    vm.lang = lang
    vm.config = config
    vm.ADDRESS_FIELDS = ADDRESS_FIELDS

    const handleErrorResponse = response => {
      if (response.error === 40304) {
        registrationService.promptLogin(
          $state.current.name,
          '',
          '',
          '',
          lang.values.loginToContinue
        )
      } else {
        vm.error = response.data ? response.data.message : response.message
      }
    }

    vm.$onInit = () => {
      vm.saveToDb = !!(vm.saveToDb || _.isNil(vm.saveToDb))
    }

    vm.addressChosenCallback = address => {
      // address can be manually edited by user so should update alone
      if (address.address1) {
        vm.data.address1 = address.address1
        $scope.addressForm.address1.$setValidity('address1', true)
      }
      if (address.city || address.province_short || address.postal_code) {
        vm.data.city = address.city
        $scope.addressForm.city.$setTouched()
        address.city && $scope.addressForm.city.$setValidity('city', true)
        vm.data.province = address.province_short
        $scope.addressForm.province.$setTouched()
        address.province_short && $scope.addressForm.province.$setValidity('province', true)
        vm.data.postal_code = address.postal_code
        $scope.addressForm.postalCode.$setTouched()
        address.postal_code && $scope.addressForm.postalCode.$setValidity('postalCode', true)
      }

      $timeout(() => {
        const missingFields = document.getElementsByClassName('address-input-field ng-invalid')
        if (missingFields.length) {
          missingFields[0].focus()
        }
      })
    }

    vm.saveAddress = () => {
      delete vm.error
      delete vm.message

      if (!vm.saveToDb) {
        const address = vm.data

        vm.onSave({ address })
        return $q.resolve(address)
      }

      return addressList.saveAddress(vm.data).then(address => {
        addressList.unMarkAddressValidated(address.id)
        vm.message = 'Saved changes successfully!'
        vm.onSave({ address })
        return address
      })
    }

    vm.normalizeData = () => {
      // Normalize postal code
      vm.data.postal_code = vm.data.postal_code?.toUpperCase().replace(/\s/g, '')
    }

    vm.validateAddressAndSave = () => {
      if ($scope.addressForm.$invalid) {
        $scope.addressForm.address1.$setTouched()
        $scope.addressForm.city.$setTouched()
        $scope.addressForm.postalCode.$setTouched()
        $scope.addressForm.firstName.$setTouched()
        $scope.addressForm.lastName.$setTouched()
        $scope.addressForm.phoneNumber.$setTouched()

        return
      }

      vm.normalizeData()

      if (vm.validateAddress) {
        addressList
          .validateAddress(vm.data)
          .then(() => vm.saveAddress())
          .then(address => addressList.markAddressValidated(address.id))
          .catch(error => error && handleErrorResponse(error))
      } else {
        vm.saveAddress().catch(error => error && handleErrorResponse(error))
      }
    }

    vm.cancelAddressEdit = () => vm.onCancel()

    vm.isFieldRequired = field => !vm.optionalFields || !vm.optionalFields.includes(field)
  },
})
