import _ from 'lodash'
import angular from 'angular'
import { v4 as uuidv4 } from 'uuid'

import { SHOPPING_MODES } from '@app/angular/constants/context'
import { ITEM_QUANTITY_IDENTIFIER } from '@app/angular/services/modules/analytics/analyticsConstants'

// Exported for use if someone does not want to use the buffer - They can use cartAnalytics.itemChange
export const ACTIONS = {
  ADD: 'ADD',
  REMOVE: 'REMOVE',
}

const PRODUCT_CONFIG_TYPES = {
  PREP_STYLE: 'prep_style',
}

const EVENTS = {
  [ACTIONS.ADD]: 'store.add_to_cart',
  [ACTIONS.REMOVE]: 'store.remove_from_cart',
}

const BUFFER_FLUSH_TIMEOUT = 2000 // Flush the cart change buffer after this ms

/* This html attribute is to indicate the root DOM element where the collection of
   add-to-cart buttons are grouped together for calculating corner distance
   purposes. */
const ANALYTIC_SCOPE_ATTR = 'data-analytic-scope'

/**
 * The addItem and removeItem methods will buffer events and flush them when:
 *  1. A different product is added/removed
 *  2. The same product is added/removed instead of removed/added
 *  3. After 2 seconds of not adding or removing
 */
export default angular
  .module('CartAnalyticsServiceModule', [])
  .service('cartAnalytics', function ($timeout, analytics, auth, cartService) {
    const service = this
    let buffer = null

    const analyticScopes = {}

    service.createNewAnalyticScope = () => {
      const scope = uuidv4()
      analyticScopes[scope] = []
      return scope
    }

    service.destroyAnalyticScope = scope => {
      if (!analyticScopes) {
        return
      }
      delete analyticScopes[scope]
    }

    /**
     * Traverse up in the DOM tree until finding an element with an attr same as the value
     * in ANALYTIC_SCOPE_ATTR. The element with this attr contains a grid of add-to-cart
     * buttons that we want to track as a grid.
     *
     * @param {object} element    - The DOM element to start the traversal
     *
     * @returns {string}          - The scope identifier to find coordinates later
     */
    service.findAnalyticScope = element => {
      while (element && element.hasAttribute && !element.hasAttribute(ANALYTIC_SCOPE_ATTR)) {
        element = element.parentElement
      }
      if (element && element.hasAttribute(ANALYTIC_SCOPE_ATTR)) {
        return element.getAttribute(ANALYTIC_SCOPE_ATTR)
      }
      return null
    }

    service.registerAddToCart = (scope, atc) => {
      if (!scope || !analyticScopes[scope] || !analyticScopes[scope].push) {
        return
      }
      analyticScopes[scope].push(atc)
    }

    /**
     * Finds the coordinates of the clicked item within a collection. For example, clicking
     * the forth item on the second row of the search results would return {x:3, y:1, corner:4}.
     * The coordinates are 0-based. Corner is the manhattan distance to the origin.
     *
     * @param {string} scope      - The scope id generated by the createNewAnalyticScope above
     * @param {number} index      - The index of the item clicked in the scope
     *
     * @returns {object}          - An object with properties "x", "y" and "corner". Returned
     *                              undefined if index is undefined or scope not setup properly
     */
    service.getCornerDistanceInScope = (scope, index) => {
      const cells = analyticScopes[scope]
      if (!scope || !cells || index === undefined || !cells.length) {
        return
      }

      /* This algorithm iterate through the add-to-cart buttons in the
         pre-scanned scope, if the top property has not changed, we
         assume the button is on the same row and increment the col.
         If the top property increases, we increment row and reset col.
      */
      let i = 1
      let lastTop = cells[0].getBoundingClientRect().top
      let row = 0
      let col = 0
      while (i <= index && i < cells.length) {
        const current = cells[i]
        const { top } = current.getBoundingClientRect()
        if (lastTop < top) {
          row += 1
          col = 0
          lastTop = top
        } else {
          col += 1
        }
        i += 1
      }
      return { x: col, y: row }
    }

    /**
     * @Private
     * Buffers the current dataset so that the delta will be combined
     *
     * @param {object} [info]     - Object containing "product, index, etc etc" information
     *                              (The action and quantity must not be contained here!)
     * @param {*} action          - The action (add or remove) via constants
     * @param {number} quantity   - Quantity of the item added to the cart
     *
     * If info is nullish, it will result in a buffer flush if the buffer is present
     *
     * @returns {undefined}
     */
    const _writeToBuffer = (info = null, action = null, quantity = 0) => {
      if (!buffer) {
        buffer = { info, action, quantity }
      } else if (_.isEqual(buffer.info, info) && buffer.action === action) {
        buffer.quantity += quantity
      } else {
        $timeout.cancel(buffer._timer)
        this.itemChange({ ...buffer, ...buffer.info })
        buffer = info ? { info, action, quantity } : null
      }

      if (buffer) {
        $timeout.cancel(buffer._timer)
        buffer._timer = $timeout(() => _writeToBuffer(), BUFFER_FLUSH_TIMEOUT)
      }
    }

    /**
     * @Private
     * Calculates cart event quantities
     *
     * @param {object} cart     - Cart from the CartService
     * @param {object} product  - The product that was actioned to the cart
     * @param {number} quantity - Quantity of the item actioned to the cart
     * @param {number} action   - The action constant
     *
     * @returns {undefined}
     */
    const _getCartQuantityDelta = (prevCart, prevCartItem, product, quantity, action) => {
      const cartItem = cartService.getItem(product)
      const isOrderedByWeight = product.order_by_weight
      const deltaSign = action === ACTIONS.ADD ? '+' : '-'

      // Weight selection is not incremental - *quantity* is the final quantity
      if (isOrderedByWeight && cartItem) {
        const isPrepStyle = product.product_config?.config_type === PRODUCT_CONFIG_TYPES.PREP_STYLE
        let previousQuantity = prevCartItem?.quantity || 0
        let delta = quantity

        if (isPrepStyle) {
          previousQuantity = prevCart.items
            .filter(item => item.id === product.id)
            .reduce((quantity, item) => item.quantity + quantity, 0)
        } else if (previousQuantity !== 0 && quantity !== previousQuantity) {
          delta = Math.abs(quantity - previousQuantity)
        }

        return {
          delta: `${deltaSign}${delta}`,
        }
      }

      const delta = `${deltaSign}${quantity}`

      return {
        delta,
      }
    }

    /**
     * @Private
     * Generates the analytics payload
     *
     * @param {object} product       - The product that was added to the cart
     * @param {number} quantity      - Quantity of the item added to the cart
     * @param {number} index         - Position in in the list from where the product was added
     * @param {string} location      - The originating location
     * @param {object} locationValue - Info about the item involved in the location
     * @param {number} action        - The action constant
     *
     * @returns {object}
     */
    const _getAnalyticsPayload = ({
      product,
      quantity,
      prevCart,
      prevCartItem,
      index,
      location,
      locationValue,
      action,
      extraData,
    }) => {
      const { corner_distance } = extraData || {}
      const { cart } = cartService
      const { delta } = _getCartQuantityDelta(prevCart, prevCartItem, product, quantity, action)
      const previousQuantity = prevCartItem?.quantity || 0
      const default_uom = product.order_by_weight
        ? ITEM_QUANTITY_IDENTIFIER.WEIGHTED_LB
        : ITEM_QUANTITY_IDENTIFIER.UNWEIGHTED
      const uom = product.display_uom ? product.display_uom : default_uom

      const analyticsPayload = {
        cart_id:
          auth.getContext().intent !== SHOPPING_MODES.INSTORE_LIST && cart.cart_instance_id
            ? cart.cart_instance_id
            : cart.id,
        uom,
        source: location,
        source_value: locationValue,
        ...analytics.getProductInfoAnalytics(product, index, corner_distance),
        ...extraData,
      }

      if (
        uom === ITEM_QUANTITY_IDENTIFIER.WEIGHTED_LB ||
        uom === ITEM_QUANTITY_IDENTIFIER.WEIGHTED_KG
      ) {
        analyticsPayload.delta_weight = delta
        analyticsPayload.previous_weight = previousQuantity
      } else {
        analyticsPayload.delta = delta
        analyticsPayload.previous_quantity = previousQuantity
      }

      let impressionData = {}
      if (action === ACTIONS.ADD) {
        // `cart_is_empty` is true if the cart was empty prior to adding the current product.
        analyticsPayload.cart_is_empty = cart.items.length === 1 && cart.items[0].quantity === 1

        // Include relevant Impression data
        const placementImpression = analytics.getActivePlacementImpression()
        const featuredProductImpression = analytics.getFeaturedProductImpression(product.id)
        impressionData = {
          ...placementImpression,
          ...featuredProductImpression,
          impression_id:
            featuredProductImpression?.impression_id || placementImpression?.impression_id,
        }
      }

      return { ...analyticsPayload, ...impressionData }
    }

    /**
     * BATCHES analytics for *adding* an item to the cart
     *
     * @param {object} product       - The product that was added to the cart
     * @param {number} quantity      - Quantity of the item added to the cart
     * @param {number} index         - Position in in the list from where the product was added
     * @param {string} location      - The originating location
     * @param {object} locationValue - Info about the item involved in the location
     *
     * @returns {undefined}
     */
    service.addItem = (product, quantity, index, location, locationValue, extraData = {}) => {
      const { cart } = cartService
      const prevCartItem = { ...cartService.getItem(product) }
      _writeToBuffer(
        { product, index, prevCart: cart, prevCartItem, location, locationValue, extraData },
        ACTIONS.ADD,
        quantity
      )
    }

    /**
     * BATCHES analytics for *removing* an item from the cart
     *
     * @param {object} product       - The product that was added to the cart
     * @param {number} quantity      - Quantity of the item added to the cart
     * @param {number} index         - Position in in the list from where the product was added
     * @param {string} location      - The originating location
     * @param {object} locationValue - Info about the item involved in the location
     *
     * @returns {undefined}
     */
    service.removeItem = (product, quantity, index, location, locationValue, extraData = {}) => {
      const { cart } = cartService
      const prevCartItem = { ...cartService.getItem(product) }
      _writeToBuffer(
        { product, index, prevCart: cart, prevCartItem, location, locationValue, extraData },
        ACTIONS.REMOVE,
        quantity
      )
    }

    /**
     * Handle cart update quantity analytics. Internally this method calls
     * *addItem* and *removeItem* depending if the new *quantity* is greater or
     * less than the previous quantity of the item inside the cart
     *
     * @param {array} args  - Args passed to updateQuantity:
     *                        [0] {object} product       - The product that was removed to the cart
     *                        [1] {number} quantity      - Quantity of the item removed from the cart
     *                        [2] {number} index         - Position in in the list from where the product was removed
     *                        [3] {string} location      - The originating location
     *                        [4] {object} locationValue - Info about the item involved in the location
     */
    service.updateQuantity = (...args) => {
      const [product, quantity] = args
      const cartItem = cartService.getItem(product)
      const cartQuantity = cartItem?.quantity
      const isAdding = quantity > cartQuantity

      if (isAdding) {
        this.addItem.apply(this, args)
        return
      }

      this.removeItem.apply(this, args)
    }

    /**
     * @Optional
     * Triggers analytics for an item to the cart
     *
     * @param {object} details - Object of information:
     *                          {object} product       - The product that was removed to the cart
     *                          {number} quantity      - Quantity of the item removed from the cart
     *                          {number} index         - Position in in the list from where the product was removed
     *                          {string} location      - The originating location
     *                          {object} locationValue - Info about the item involved in the location
     * @returns {undefined}
     */
    service.itemChange = details => {
      analytics.logSegmentEvent(EVENTS[details.action], {
        ..._getAnalyticsPayload(details),
        ...(details.action === ACTIONS.ADD &&
          analytics.getFeaturedProductImpression(details.product.id)),
      })
      if (details.action === ACTIONS.ADD) {
        analytics.logProductAddToCart(details.product, details.location, auth.getContext().intent)
      } else if (details.action === ACTIONS.REMOVE) {
        analytics.logProductRemoveFromCart(
          details.product,
          details.location,
          auth.getContext().intent
        )
      }
    }
  })
