import angular from 'angular'
import getApiPrefix from '@app/utils/getApiPrefix'
import urlParser from '@app/utils/urlParser'

const apiPrefix = getApiPrefix()

export default angular
  .module('canvasServicesModule', [])
  .service('canvas', function canvas($http, Restangular) {
    const service = this

    // Log interaction for user_events
    service.logInteraction = function (content) {
      if (content._links && content._links.interaction) {
        $http.post(apiPrefix + content._links.interaction.href, content._links.interaction.data)
      }
    }
    // Log impression for user_events
    service.logImpression = function (content) {
      if (content && content._links && content._links.impression) {
        $http.post(apiPrefix + content._links.impression.href, content._links.impression.data)
      }
    }
    // Log pageviews of a page specified
    service.logPageView = function (endpoint) {
      return Restangular.all('user_events').all(endpoint).post({})
    }
  })
  .directive('contentHref', function () {
    return {
      scope: {
        contentHref: '=',
      },
      controller($scope, $element, $state, config, mobileService, utilService) {
        const unwatch = $scope.$watch('contentHref', value => {
          if (value && $scope.contentHref && $scope.contentHref.target) {
            const isExternalLink = !utilService.isLinkInternal($scope.contentHref.target.url)

            if ($scope.contentHref.target.type === 'url') {
              if (isExternalLink) {
                $element.attr('href', $scope.contentHref.target.url)
              } else {
                const parsedUrlParams = urlParser($scope.contentHref.target.url)
                $element.attr(
                  'href',
                  utilService.getPrependHashIfNeeded(
                    parsedUrlParams.pathname + parsedUrlParams.search
                  )
                )
              }
              if (config.openExternalLinksNewTab && isExternalLink) {
                $element.attr('target', '_blank')
              }
            } else {
              switch ($scope.contentHref.target.type) {
                case 'collection':
                  $element.attr(
                    'href',
                    $state.href('main.shop.collection', {
                      collection_id: $scope.contentHref.target.collection.id,
                    })
                  )
                  break

                case 'product':
                  $element.attr(
                    'href',
                    $state.href('main.product', { id: $scope.contentHref.target.product.id })
                  )
                  break
              }
            }

            unwatch()
          }
        })
      },
    }
  })
  .directive('contentAlt', function () {
    return {
      scope: {
        contentAlt: '=',
      },
      controller($scope, $element) {
        // a11y - used to update the alt attribute for linked images to indicate
        // to screenreader users the purpose of the image link
        const unwatch = $scope.$watch('contentAlt', value => {
          const target = value && $scope.contentAlt && $scope.contentAlt.target
          if (target) {
            switch (target.type) {
              case 'collection':
                if (target.collection.description) {
                  $element.attr('alt', target.collection.description)
                }
                break
              case 'product':
                if (target.product.description) {
                  $element.attr('alt', target.product.description)
                }
                break
            }

            unwatch()
          }
        })
      },
    }
  })
