import angular from 'angular'

const FIRST_DATA_INFORMATION = {
  name: 'FirstDataV2',
  key: 'auth_capture',
  serviceName: 'firstDataV2Payment',
  newCheckoutCssClass: 'provider-generic_styling',
}

export default angular
  .module('FirstDataPaymentV2ServiceModule', [])

  .config(paymentGatewayProvider => {
    paymentGatewayProvider.registerGateway({
      template: require('./firstDataIntegration.html'),
      ...FIRST_DATA_INFORMATION,
    })
  })

  .service('firstDataV2Payment', function firstDataV2Payment($q, config, defaultPayments) {
    const service = this

    service.information = FIRST_DATA_INFORMATION
    service.status = {}
    service.checkout = defaultPayments.checkout
  })
