import React, { Fragment, useState, useEffect } from 'react'
import {
  createStyles,
  Button,
  Text,
  GridContainer,
  GridItem,
  FormControl,
  FormLabel,
  Select,
  TextField,
  FormHelpText,
} from '@instacart/cocktail'
import PropTypes from 'prop-types'
import dayjs from 'dayjs'
import relativeTime from 'dayjs/plugin/relativeTime'
import { SUBSCRIPTION_PATHS } from '../constants'
import Header from '../Header'

dayjs.extend(relativeTime)

const useStyles = createStyles(tokens => ({
  root: {
    paddingLeft: tokens.size.spacing.medium,
    paddingRight: tokens.size.spacing.medium,
  },
  horizontalCenterAlign: {
    display: 'flex',
    justifyContent: 'center',
  },
  subscriptionsContainer: {
    padding: tokens.size.spacing.small,
    borderRadius: tokens.border.radius.regular,
    backgroundColor: tokens.color.neutral.grey1,
    marginTop: tokens.size.spacing.small,
    marginBottom: tokens.size.spacing.small,
  },
  radioContainer: {
    display: 'flex',
    textTransform: 'capitalize',
    marginBottom: tokens.size.spacing.small,
    '&:last-child': {
      marginBottom: tokens.size.spacing.none,
    },
  },
  inputLabel: {
    marginLeft: tokens.size.spacing.xxSmall,
  },
  formLabel: {
    fontSize: tokens.size.font.large,
  },
  header: {
    padding: tokens.size.spacing.medium,
    marginBottom: tokens.size.spacing.xxLarge,
  },
  disclaimer: {
    maxWidth: 676,
    color: tokens.color.text.secondary,
    fontFamily: tokens.font.family.secondary,
    textAlign: 'left',
    marginBottom: tokens.size.spacing.large,
  },
}))

const currentDate = new Date()

const formatDays = days => {
  const expiryDate = dayjs(currentDate).add(days, 'day')
  return (
    dayjs(currentDate)
      .to(dayjs(expiryDate), true)
      // 1 Month instead of A Month (default)
      .replace(/^(a|an)/i, '1')
  )
}

const getMonthString = month => dayjs(new Date(0, month)).format('MMMM')

const nextTenYears = new Array(10).fill(0).map((_, i) => ({
  value: currentDate.getFullYear() + i,
  label: `${currentDate.getFullYear() + i}`,
}))
const listOfMonths = new Array(12).fill(0).map((_, i) => ({ value: i, label: getMonthString(i) }))

const defaultLang = {
  disclaimer: '',
  form: {
    selectSubscription: 'Choose a subscription',
    emailLabel: "Recipient's email",
    emailPlaceholder: 'example@instacart.com',
    submit: 'Continue to Payment',
    dateLabel: 'Delivery Date',
    customMessageLabel: 'Personalize your gift message',
    customMessageCharacters: 'Characters',
    errors: {
      dateSelect: 'Date cannot be in the past',
    },
  },
}

const SubscriptionSelection = props => {
  const styles = useStyles(props)
  const {
    onStepComplete,
    onSelectionComplete,
    path,
    maxMessageLength,
    showDateSelection,
    subscriptions,
    i18n,
  } = props

  const lang = { ...defaultLang, ...i18n }

  const [formData, setFormData] = useState({
    email: '',
    selectedSubscription: subscriptions[0] && subscriptions[0].id.toString(),
    selectedYear: currentDate.getFullYear(),
    selectedMonth: currentDate.getMonth(),
    selectedDay: currentDate.getDate(),
    customMessage: '',
    errors: {
      dateSelect: '',
      customMessage: false,
    },
  })
  const [daysForSelectedMonth, setDaysForSelectedMonth] = useState([])

  useEffect(() => {
    const newDaysList = new Array(
      dayjs(new Date(formData.selectedYear, formData.selectedMonth)).daysInMonth()
    )
      .fill(0)
      .map((_, i) => ({ value: i + 1, label: `${i + 1}` }))

    setDaysForSelectedMonth(newDaysList)
  }, [formData.selectedMonth, formData.selectedYear])

  useEffect(() => {
    setFormData(data => ({
      ...data,
      selectedSubscription: subscriptions && subscriptions[0] && subscriptions[0].id.toString(),
    }))
  }, [subscriptions])

  /*
   * Gets the errors present on the given form data
   *
   * @param {Object} - The form data to get errors for
   *
   * @returns {Object} errors
   * {string} errors.dateSelect - Error message to be shown if selected date is in the past. Empty if no error.
   * {boolean} errors.customMessage - True if the custom message is above the character limit. False if no error.
   */
  const getErrors = updatedFormData => {
    const selectedDate = new Date(
      updatedFormData.selectedYear,
      updatedFormData.selectedMonth,
      updatedFormData.selectedDay
    )
    const dateValid = !dayjs(selectedDate).isBefore(dayjs(currentDate), 'day')

    return {
      dateSelect: dateValid ? '' : lang.form.errors.dateSelect,
      customMessage: updatedFormData.customMessage.length > maxMessageLength,
    }
  }

  const hasErrors = () => {
    return (
      Object.values(formData.errors).reduce((foundError, error) => foundError || !!error, false) ||
      (!formData.email && path === SUBSCRIPTION_PATHS.GIFTING)
    )
  }

  const handleSubmit = event => {
    event.preventDefault()

    if (!hasErrors()) {
      const params = {
        path,
        selectedSubscription: subscriptions.find(
          subs => subs.id.toString() === formData.selectedSubscription
        ),
        customMessage: formData.customMessage,
        date: new Date(formData.selectedYear, formData.selectedMonth, formData.selectedDay),
        email: formData.email,
      }
      onSelectionComplete && onSelectionComplete(params)
      onStepComplete(params)
    }
  }

  const handleStateChange = key => {
    return event => {
      const newState = {
        ...formData,
        [key]: event.value || event.target.value,
      }
      setFormData({
        ...newState,
        errors: getErrors(newState),
      })
    }
  }

  return (
    <Fragment>
      <Header css={styles.header} i18n={i18n.header[path] || i18n.header.default} />
      <div css={styles.root}>
        <FormLabel style={styles.formLabel}>{lang.form.selectSubscription}</FormLabel>

        <form onSubmit={handleSubmit}>
          <div css={styles.subscriptionsContainer}>
            {subscriptions.map(subscription => (
              <div key={subscription.id} css={styles.radioContainer}>
                {/* TODO: Use cocktail Radio button */}
                <input
                  type="radio"
                  name="subscription"
                  value={subscription.id}
                  checked={subscription.id.toString() === formData.selectedSubscription}
                  onChange={handleStateChange('selectedSubscription')}
                />
                <Text
                  bold={subscription.id.toString() === formData.selectedSubscription}
                  css={styles.inputLabel}
                >
                  {subscription.name}
                  {
                    // eslint-disable-next-line react/jsx-no-literals
                    ' ('
                  }
                  {formatDays(subscription.days)}
                  {
                    // eslint-disable-next-line react/jsx-no-literals
                    ') - $'
                  }
                  {subscription.price}
                </Text>
              </div>
            ))}
          </div>

          <div css={styles.horizontalCenterAlign}>
            <Text
              css={styles.disclaimer}
              variant="caption"
              dangerouslySetInnerHTML={{ __html: i18n.disclaimer }}
            />
          </div>

          {path === SUBSCRIPTION_PATHS.GIFTING && (
            <GridContainer>
              <GridItem xSmall={12}>
                <GridItem medium={6} small={12}>
                  <TextField
                    value={formData.email}
                    onChange={handleStateChange('email')}
                    type="email"
                    label={lang.form.emailLabel}
                    inputSize="small"
                    placeholder={lang.form.emailPlaceholder}
                    FormLabelProps={{ style: styles.formLabel }}
                    fullWidth
                    required
                  />
                </GridItem>
              </GridItem>

              {showDateSelection && (
                <GridItem xSmall={12}>
                  <GridItem medium={6} small={12}>
                    <FormControl error={formData.errors.dateSelect.length > 0} required>
                      <FormLabel style={styles.formLabel}>{lang.form.dateLabel}</FormLabel>
                      <GridContainer spacing="xxxSmall">
                        <GridItem xSmall={7}>
                          <Select
                            value={listOfMonths.find(
                              option => option.value === formData.selectedMonth
                            )}
                            options={listOfMonths}
                            onChange={handleStateChange('selectedMonth')}
                            inputSize="small"
                            minWidth={0}
                            fullWidth
                          />
                        </GridItem>
                        <GridItem xSmall={2}>
                          <Select
                            inputSize="small"
                            value={daysForSelectedMonth.find(
                              option => option.value === formData.selectedDay
                            )}
                            onChange={handleStateChange('selectedDay')}
                            minWidth={0}
                            options={daysForSelectedMonth}
                            fullWidth
                          />
                        </GridItem>
                        <GridItem xSmall={3}>
                          <Select
                            inputSize="small"
                            value={nextTenYears.find(
                              option => option.value === formData.selectedYear
                            )}
                            onChange={handleStateChange('selectedYear')}
                            minWidth={0}
                            options={nextTenYears}
                            fullWidth
                          />
                        </GridItem>
                      </GridContainer>
                      {formData.errors.dateSelect && (
                        <FormHelpText>{formData.errors.dateSelect}</FormHelpText>
                      )}
                    </FormControl>
                  </GridItem>
                </GridItem>
              )}

              <GridItem xSmall={12} medium={8}>
                <TextField
                  error={formData.errors.customMessage}
                  value={formData.customMessage}
                  onChange={handleStateChange('customMessage')}
                  label={lang.form.customMessageLabel}
                  helpText={`${formData.customMessage.length}/${maxMessageLength} ${lang.form.customMessageCharacters}`}
                  FormHelpTextProps={{ style: { textAlign: 'right' } }}
                  FormLabelProps={{ style: styles.formLabel }}
                  multiline
                  fullWidth
                />
              </GridItem>
            </GridContainer>
          )}

          <div css={styles.horizontalCenterAlign}>
            <Button type="submit" disabled={hasErrors()}>
              {lang.form.submit}
            </Button>
          </div>
        </form>
      </div>
    </Fragment>
  )
}

SubscriptionSelection.defaultProps = {
  maxMessageLength: 250,
  showDateSelection: false,
}

SubscriptionSelection.propTypes = {
  /** Maximum length of the custom message */
  maxMessageLength: PropTypes.number,
  /** Whether to display the date selection for gifting */
  showDateSelection: PropTypes.bool,
  /** An array of subscription objects the user is selecting between */
  subscriptions: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number.isRequired,
      name: PropTypes.string.isRequired,
      days: PropTypes.number.isRequired,
      price: PropTypes.number.isRequred,
      status: PropTypes.boolean,
      created: PropTypes.string,
      modified: PropTypes.string,
    })
  ).isRequired,
  /** Language object with form labels and errors */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Function to call when user selects subscription plan */
  onSelectionComplete: PropTypes.func,
  /** Function to call when user submits form */
  onStepComplete: PropTypes.func,
  /** Whether the user is gifting or buying the subscription */
  path: PropTypes.oneOf(Object.values(SUBSCRIPTION_PATHS)),
}

export default SubscriptionSelection
