import angular from 'angular'

export default angular
  .module('SnapEBTAddModalControllerModule', [])
  .controller(
    'SnapEBTAddModalController',
    function ($rootScope, $scope, $uibModalInstance, config, ebtCardService, lang, mobileService) {
      Object.assign($scope, { lang, mobileService })
      $scope.ebtCardNumber = ''
      $scope.validEbtCardInput = false
      $scope.showAddError = false
      $scope.loadingRequest = false

      $scope.validateCard = () => {
        $scope.validEbtCardInput = $scope.ebtCardNumber?.length >= 16
      }

      $scope.addCard = function () {
        $scope.loadingRequest = true
        $scope.showAddError = false

        ebtCardService
          .addCard($scope.ebtCardNumber)
          .then(() => {
            $uibModalInstance.close()
          })
          .catch(() => {
            $scope.showAddError = true
          })
          .finally(() => {
            $scope.loadingRequest = false
          })
      }
    }
  )
