import React, { memo, forwardRef } from 'react'
import PropTypes from 'prop-types'
import { Text, Button, createStyles } from '@instacart/cocktail'

import { TIMESLOT_POLYTYPES } from '../constants'

import { formatDayOfTheWeek, formatDayOfTheMonth } from '../utils'

const useStyles = createStyles(tokens => ({
  root: {},
  button: {
    background: tokens.color.neutral.grey1,
    color: tokens.color.brand.primary.dark,
    height: tokens.size.layout.xLarge,
    maxWidth: tokens.size.layout.xLarge + 16,
    minWidth: tokens.size.layout.xLarge,
    whiteSpace: 'normal',
    '&:hover': {
      backgroundColor: tokens.color.brand.primary.light,
    },
  },
  buttonUnavailable: {
    // Unfortunately, !important is required to override a web-frontend button style on `@keyframes appear`.
    opacity: '0.5 !important',
  },
  buttonSelected: {
    background: tokens.color.brand.primary.dark,
    color: tokens.color.neutral.white,
    '&:hover': {
      backgroundColor: tokens.color.brand.primary.dark,
    },
  },
  buttonNotSelected: {
    '&:active': {
      backgroundColor: 'transparent',
      borderColor: tokens.color.brand.primary.dark,
      color: tokens.color.brand.primary.dark,
    },
  },
}))

const defaultLang = {
  today: 'Today',
  fastestPossible: 'Fastest Possible',
  unavailable: 'Unavailable',
}

const NAVIGATION_KEYS = [
  'Enter',
  'Spacebar',
  ' ',
  'ArrowRight',
  'ArrowDown',
  'ArrowLeft',
  'ArrowUp',
]

const Day = forwardRef((props, ref) => {
  const styles = useStyles(props)
  const { isSelected, day, i18n, onSelected, isUnavailable, ...restProps } = props
  const lang = { ...defaultLang, ...i18n }
  const onKeyDown = event => {
    if (NAVIGATION_KEYS.includes(event.key)) {
      return onSelected(event)
    }
  }

  const dayTabLabel = `${formatDayOfTheMonth(day, 2)}${
    isUnavailable ? ` (${lang.unavailable})` : ''
  }`

  const tabProps = {
    ref,
    onKeyDown,
    role: 'tab',
    onClick: onSelected,
    'aria-selected': isSelected,
    css: [
      styles.button,
      isSelected ? styles.buttonSelected : styles.buttonNotSelected,
      styles.root,
      isUnavailable && styles.buttonUnavailable,
    ],
    ...restProps,
  }

  if (!isSelected) tabProps.tabIndex = -1

  return (
    <Button aria-label={dayTabLabel} {...tabProps}>
      {day === TIMESLOT_POLYTYPES.ASAP ? (
        <Text data-test="day-asap">{lang.fastestPossible}</Text>
      ) : (
        <span>
          <Text variant="large" bold data-test="day-of-week">
            {formatDayOfTheWeek(day, lang.today)}
          </Text>
          <Text variant="caption" data-test="day-of-month">
            {formatDayOfTheMonth(day)}
          </Text>
        </span>
      )}
    </Button>
  )
})

Day.propTypes = {
  /** The day to display */
  day: PropTypes.string.isRequired,
  /** If the day is selected */
  isSelected: PropTypes.bool,
  /** If the day does not have any available timeslots */
  isUnavailable: PropTypes.bool,
  /** Custom lang object */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Function to call when a day is selected */
  onSelected: PropTypes.func,
}

export default memo(Day)
