import angular from 'angular'

export default angular.module('chatIntegration', []).run(function ($window, config) {
  const head = document.head || document.getElementsByTagName('head')

  // Custom Watson code
  const initWatson = () => {
    $window.loadWatsonAssistantChat(config.chatIntegration.parameters).then(function (instance) {
      instance.render()
    })
  }

  // Custom bot copy code
  const initBotCopy = onLoadCallback => {
    const outerScriptTag = document.createElement('script')

    // Set parameters on the outerscript tag
    if (config.chatIntegration.parameters) {
      Object.keys(config.chatIntegration.parameters).forEach(param => {
        outerScriptTag.setAttribute(param, config.chatIntegration.parameters[param])
      })
    }

    outerScriptTag.onload = onLoadCallback
    head.insertBefore(outerScriptTag, head.firstChild)

    // Load second script tag inside outer tag pointing to src.
    const scriptTag = document.createElement('script')
    scriptTag.src = config.chatIntegration.scriptSrc
    scriptTag.type = 'text/javascript'
    scriptTag.async = true
    outerScriptTag.appendChild(scriptTag)
  }

  // Injects parameters from chatIntegration.parameters to attributes on the
  // script element; possibly unsafe
  const DATA_ATTR_REGEX = /^data-(.*)$/

  const parametersToAttributes = el => {
    if (!config.chatIntegration.parameters) {
      return
    }

    Object.keys(config.chatIntegration.parameters).forEach(key => {
      if (key === 'id' || DATA_ATTR_REGEX.test(key)) {
        el.setAttribute(key, config.chatIntegration.parameters[key])
      }
    })
  }

  // Generic script load function
  const loadScript = ({ onLoadCallback, attributesCallback }) => {
    const scriptTag = document.createElement('script')
    scriptTag.src = config.chatIntegration.scriptSrc

    if (onLoadCallback) {
      scriptTag.onload = onLoadCallback
    }

    if (attributesCallback) {
      attributesCallback(scriptTag)
    }

    head.insertBefore(scriptTag, head.firstChild)
  }

  const start = () => {
    switch (config.chatIntegration.type) {
      case 'botcopy':
        initBotCopy()
        break
      case 'watson':
        loadScript({ onLoadCallback: initWatson })
        break
      case 'rasa':
        loadScript({ attributesCallback: parametersToAttributes })
        break
    }
  }

  if (config.chatIntegration?.enabled) start()
})
