import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'

export default angular
  .module('toggleEComLightServicesModule', [])
  .service(
    'toggleEComLightService',
    function eComLightService(
      $rootScope,
      $state,
      auth,
      cartListTransferService,
      cartService,
      config,
      eComLightService,
      shoppingContextService,
      shoppingSelectorService
    ) {
      if (config.eComLight.enabled) {
        const eComLightFulfillmentType = config.eComLight.fulfillmentType

        // toggleEComLightMode mirrors some of the logic in contextSelectorController and compareCartService,
        //  but is simplified to allow a more efficient switch for a user to/from eComLight mode,
        //  in addition to allowing the "deep-linking" behaviour straight to the cartToListMigration screen that is desired
        this.toggleEComLightMode = () => {
          const initialContext = auth.getContext().intent
          const updatedContext =
            eComLightService.inEComLightMode &&
            config.shoppingSelector[SHOPPING_MODES.INSTORE_LIST].exists
              ? SHOPPING_MODES.INSTORE_LIST
              : eComLightService.inEComLightMode &&
                config.shoppingSelector[SHOPPING_MODES.PICKUP].exists
              ? SHOPPING_MODES.PICKUP
              : SHOPPING_MODES.CATERING

          // Current number of categorized products in user's list/cart (this will exclude custom and uncategorized products)
          const categorizedProducts = cartService.getAllStoreProductsInCart()
          const numCategorized = categorizedProducts.length
          const numCustom = cartService.countCustomItems()
          const numTotal = numCategorized + numCustom

          const transferData = {
            numCustom,
            numTotal,
            numTransferable: 0, // We assume numTransferable is 0 and transferableProducts is [] for eComLight mode
            transferableProducts: [],
          }

          const switchContextAndRedirectIfNecessary = () =>
            auth.setContext({ intent: updatedContext }).then(() => {
              const { redirectTo, redirectParams } = config.shoppingSelector.catering
              if (updatedContext === SHOPPING_MODES.CATERING) {
                $state.go(redirectTo, redirectParams, { reload: true })
              } else {
                $state.go('main.home', {}, { reload: true })
              }
            })

          if (numTotal === 0) {
            // Skip the cartToListMigration if they have no products to migrate
            return switchContextAndRedirectIfNecessary()
          }
          shoppingSelectorService.open('cartToListMigration')
          shoppingContextService.setSelectedFulfillmentType(
            updatedContext === eComLightFulfillmentType ? SHOPPING_MODES.CATERING : updatedContext
          )

          // Use cartListTransferService to toggle between eComLight mode
          return cartListTransferService
            .initializeTransfer({
              initialContext,
              updatedContext,
              transferData,
            })
            .then(switchContextAndRedirectIfNecessary)
            .catch(() => {
              // User "cancelled" context change during cart compare or transfer modal; close the context selector modal.
              shoppingSelectorService.closeModal()
            })
        }

        eventEmitter.on(EVENT_TYPES.TOGGLE_CATERING_MODE, this.toggleEComLightMode)
      }
    }
  )
