/* eslint-disable max-lines */

import _ from 'lodash'
import angular from 'angular'
import { POWERED_BY } from '@app/angular/constants/instacart'
import {
  auth as authService,
  order as enterpriseServiceOrder,
  product as enterpriseProductService,
} from '@instacart/enterprise-services'
import { utils as timeslotUtils } from '@instacart/enterprise-timeslots'
import { CHECKOUT_STEP_IDS, FULFILLMENT_TYPES } from '@app/angular/constants/checkout'
import { ORDER_STATUS } from '@app/angular/constants/orders'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { CART_TYPES } from '@app/angular/constants/cart.js'
import { AUTH_TYPES, PAYMENT_TYPES } from '@app/angular/constants/payments'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { OneCartActions } from '@app/services/oneCart/types'
import cancelOrderModalTemplate from './views/cancelOrderModal.html'

const isNewFirstData = paymentGateway => paymentGateway.getByName('FirstDataV2')

export default angular
  .module('OrderControllerModule', [])
  .controller(
    'OrderController',
    function (
      $filter,
      $q,
      $rootScope,
      $sce,
      $scope,
      $state,
      $stateParams,
      $timeout,
      $uibModal,
      auth,
      cartService,
      catalogCategoriesService,
      checkoutService,
      config,
      configurableProductService,
      eComLightService,
      errorModalService,
      imageURLService,
      instacartService,
      lang,
      list,
      mobileService,
      oneCartService,
      order,
      orderFeesService,
      orderService,
      payment,
      paymentGateway,
      productDetailsModal,
      registrationService,
      setPageHeaderTitle,
      utilService
    ) {
      const { message, error } = $stateParams

      Object.assign($scope, {
        $rootScope,
        CHECKOUT_STEP_IDS,
        ORDER_STATUS,
        SHOPPING_MODES,
        CART_TYPES,
        catalogCategoriesService,
        configurableProductService,
        error,
        imageURLService,
        instacartService,
        lang,
        message,
        mobileService,
        oneCartService,
        order,
        payment,
        productDetailsModal,
        utilService,
        shoppingIntent: auth.getContext().intent,
        selectedItems: [],
        selectedMap: {},
        isOrderAddedToCart: { top: false, bottom: false },
        orderTotalsLines: [],
      })

      // order.items is a list of a single order object containing ic_order_redirect_id.
      if (oneCartService.enabled() && order.items && order.items.length > 0) {
        $rootScope.isAppLoading = true
        const currentOrder = order.items[0]
        authService.api.auth
          .getOneCartOrderRedirect(currentOrder.ic_order_redirect_id)
          .then(resp => {
            oneCartService.redirectToPBI(resp.location, resp.auth_token, {
              action: OneCartActions.ORDER_STATUS,
              orderId: currentOrder.ic_order_id,
            })
          })
          .catch(() => ($rootScope.isAppLoading = false))
        return
      }

      if (config.orderHistoryView.showOrderTracker && !mobileService.isMobile) {
        setPageHeaderTitle(`${lang.values.order} ${order.id}`)
      }

      const getRewardDiscounts = source =>
        Object.keys(source?.reward_discounts || []).map(rewardKey => ({
          label: lang.values.rewards?.[rewardKey]?.label || rewardKey,
          value: -Math.abs(parseFloat(source.reward_discounts?.[rewardKey])),
        }))

      $scope.selectAllCheckbox = {
        value: false,
      }

      $scope.outOfStockWarningPopover = $sce.trustAsHtml(
        `<span class="icon-warning add-to-cart__warning" aria-hidden="true"></span> ${lang.values.outOfStockDescription}`
      )

      $scope.isItemOOS = item =>
        !enterpriseProductService.utils.isProductAvailable(item.store_product)

      $scope.estimatedRewards = getRewardDiscounts(order.estimated_totals)

      $scope.getFeeLabel = fee => {
        return orderService.fulfillmentLabel(fee, order.fulfillment_type)
      }

      // lineNumber was deprecated in favor of line_number for API key naming consistency.
      // This check can be removed once endpoints return line_number.
      $scope.getItemLineNumber = item => item.line_number || item.lineNumber

      _.each($scope.order.order_items, item => {
        $scope.selectedMap[`${item.store_product.id}-${$scope.getItemLineNumber(item)}`] = false
      })
      _.each($scope.order.additional_order_items, item => {
        $scope.selectedMap[`${item.store_product.id}-${$scope.getItemLineNumber(item)}`] = false
      })

      const addReorderableTag = items =>
        _.map(items, item => {
          const isReorderable =
            _.isNil(item.product_config) ||
            _.isNil(item.store_product.product_config) ||
            _.isNil(item.store_product.product_config.status) ||
            item.store_product.product_config.status
          return { ...item, isReorderable }
        })

      $scope.order.order_items = addReorderableTag($scope.order.order_items)
      $scope.order.additional_order_items = addReorderableTag($scope.order.additional_order_items)

      $scope.isNewFirstData = () => isNewFirstData(paymentGateway)

      $scope.selectItem = function (item) {
        const itemIndex = _.findIndex($scope.selectedItems, selectedItemsItem => {
          if (item.product_config && selectedItemsItem.product_config) {
            return configurableProductService.isConfigEqual(
              selectedItemsItem.product_config.options,
              item.product_config.options
            )
          }
          return selectedItemsItem.store_product.id === item.store_product.id
        })

        if (itemIndex !== -1 && item.status !== 'add_on') {
          $scope.selectedItems.splice(itemIndex, 1)
          $scope.selectAllCheckbox.value = false
        } else {
          $scope.selectedItems.push(item)
        }
      }

      $scope.selectAll = function () {
        if ($scope.selectAllCheckbox.value) {
          _.each($scope.order.order_items, item => {
            const itemIndex = _.findIndex($scope.selectedItems, selectedItemsItem => {
              if (item.product_config && selectedItemsItem.product_config) {
                return configurableProductService.isConfigEqual(
                  item.product_config.options,
                  selectedItemsItem.product_config.options
                )
              }
              // eslint-disable-next-line eqeqeq
              return selectedItemsItem.store_product.id == item.store_product.id
            })

            if (
              itemIndex === -1 &&
              item.store_product.status &&
              item.isReorderable &&
              !$scope.isItemOOS(item)
            ) {
              $scope.selectedItems.push(item)
              $scope.selectedMap[
                `${item.store_product.id}-${$scope.getItemLineNumber(item)}`
              ] = true
            }
          })

          _.each($scope.order.additional_order_items, item => {
            const itemIndex = _.findIndex($scope.selectedItems, selectedItemsItem => {
              // eslint-disable-next-line eqeqeq
              return selectedItemsItem.store_product.id == item.store_product.id
            })

            if (
              itemIndex === -1 &&
              item.store_product.status &&
              item.isReorderable &&
              !$scope.isItemOOS(item)
            ) {
              $scope.selectedItems.push(item)
              $scope.selectedMap[
                `${item.store_product.id}-${$scope.getItemLineNumber(item)}`
              ] = true
            }
          })
        } else {
          $scope.selectedItems.length = 0
          _.each($scope.order.order_items, item => {
            $scope.selectedMap[`${item.store_product.id}-${$scope.getItemLineNumber(item)}`] = false
          })
          _.each($scope.order.additional_order_items, item => {
            $scope.selectedMap[`${item.store_product.id}-${$scope.getItemLineNumber(item)}`] = false
          })
        }
      }

      $scope.selectAllCheckbox.value = true
      $scope.selectAll() // Select all items by default

      const deselectAll = () => {
        $scope.selectAllCheckbox.value = false
        $scope.selectAll()
      }

      $scope.loadList = (selectedItems, location = 'top') => {
        $scope.isOrderAddedToCart = {
          top: false,
          bottom: false,
        }
        list.loadListToCart(selectedItems).then(() => {
          $scope.isOrderAddedToCart[location] = true
          deselectAll()
        })
      }

      $scope.loadAll = function () {
        list.loadListToCart($scope.order.order_items)
      }

      $scope.tip = () => instacartService.goToInstacartLink(order.ext_data.instacart_order_url)

      $scope.modifyOrder = function () {
        const unavailableItems = $scope.order.order_items.filter(item => {
          return !item.store_product.status
        })
        const formatString = items => {
          return items.map(item => item.store_product.name).toString()
        }
        const goToOrderModFlow = () =>
          config.orderHistoryView.useItemEditFlow
            ? // Using edit items flow
              $state.go('main.account.orderHistory.order.edit', { id: $scope.order.id })
            : // Classical modify
              cartService.modifyOrder($scope.order)

        if (unavailableItems.length > 0) {
          errorModalService
            .open(
              `${lang.values.orderModItemsUnavailableWarning} ${formatString(unavailableItems)}`,
              lang.values.continue,
              lang.values.cancel
            )
            .then(goToOrderModFlow)
        } else {
          goToOrderModFlow()
        }
      }

      /**
       * Performs removal of order
       *
       * @returns {Promise<T>}
       */
      function removeOrder() {
        $scope.cancellingOrder = true

        return enterpriseServiceOrder.api.orders
          .remove(order.id, order)
          .then(() => {
            const params = { ...$state.params, message: null }
            $state.go($state.current.name, params, { reload: true })
            cartService.sync()
            $scope.cancellingOrder = false
          })
          .catch(response => {
            if (response && response.error === HTTP_ERRORS.PII_UNAUTHORIZED) {
              // If PII has timed out
              registrationService.promptLogin(
                $state.current.name,
                $state.params,
                '',
                '',
                lang.values.loginToContinue
              )
              $scope.cancellingOrder = false
            } else {
              if (response.message === 'Connect::TooLateError' || response?.error_code === 40301) {
                const dismissLabel =
                  config.orderHistoryView.showOrderTracker && $scope.shoppingIntent !== 'instore'
                    ? lang.values.orderTracker.orderStatus.viewOrderProgressButton
                    : null
                errorModalService.open(
                  lang.values.orderModificationTooLate,
                  lang.values.ok,
                  dismissLabel,
                  lang.values.error,
                  false,
                  {},
                  {
                    onDismiss: () => {
                      if (
                        config.orderHistoryView.showOrderTracker &&
                        $scope.shoppingIntent !== 'instore'
                      ) {
                        $scope.goToOrderInstacartLink()
                      }
                    },
                  }
                )
              } else {
                const errorModalInstance = errorModalService.open(
                  response.message,
                  lang.values.ok,
                  null,
                  null,
                  true
                )

                errorModalInstance.closed.then(() => {
                  // Force reload to get the order's latest state
                  $rootScope.reloadState()
                })
              }
              $scope.cancellingOrder = false
            }
          })
      }

      $scope.cancelOrder = function () {
        errorModalService
          .open(
            lang.values.orderCancellationConfirmation,
            lang.values.yes,
            lang.values.no,
            lang.values.cancelOrder
          )
          .then(removeOrder)
      }

      const updateOrder = ({ auth_capture }) => {
        $scope.loading = true

        $q.resolve()
          .then(() =>
            enterpriseServiceOrder.api.orders.update(order.id, {
              payment_type: AUTH_TYPES.CAPTURE,
              auth_capture,
            })
          )
          .then(() => {
            $scope.hideUpdateCreditCardSuccess = false
          })
          .catch(() => {
            $scope.hideUpdateCreditCardError = false
          })
          .finally(() => {
            $scope.loading = false
          })
      }

      $scope.cancelOrModifyOrder = () => {
        if ($scope.isShowModifyButton()) {
          errorModalService.openCustom(cancelOrderModalTemplate, {
            doModifyImmediately: () => $scope.modifyOrder(),
            removeOrder,
            secondaryButtonLabel: config.orderHistoryView.useItemEditFlow
              ? lang.values.orderHistoryView.editItems
              : lang.values.cancelOrderModal.modifyOrder,
          })
        } else {
          $scope.cancelOrder()
        }
      }

      // Sort products by category

      const productsWithAvailabilityStatus = $scope.order.order_items
        .filter(item => item.store_product && item.store_product.categories)
        .map(item => {
          // We want to only grey-out/cross-out substituted products if they are unavailable or completely removed from the order
          // (whether the item has simply been removed or if the entire quantity has been fully substituted)
          const isUnavailable =
            !item.store_product.status || item.status === 'removed' || $scope.isItemOOS(item)
          return { ...item, isUnavailable }
        })

      catalogCategoriesService
        .getOrderedCategoriesAndProductsWithCategories(productsWithAvailabilityStatus)
        .then(({ orderedCategories, categorizedProducts }) => {
          $scope.orderedCategories = orderedCategories
          $scope.categorizedProducts = categorizedProducts
        })

      $scope.hideCreditCardForm = true
      $scope.hideUpdateCreditCardError = true
      $scope.hideUpdateCreditCardSuccess = true
      $scope.toggleCreditCardForm = () => {
        $scope.hideCreditCardForm = !$scope.hideCreditCardForm
        $scope.hideUpdateCreditCardError = true
        $scope.hideUpdateCreditCardSuccess = true
      }
      $scope.updateCreditCard = function (payment, error) {
        $scope.toggleCreditCardForm()
        if (error) {
          return ($scope.hideUpdateCreditCardError = false)
        }

        updateOrder(payment)
      }

      $scope.requestOrderAddOn = () => {
        $state.go('orderAddOn', { order: $scope.order })
      }

      $scope.showNonOrderTrackInstacartSection = () => {
        return (
          config.instacart.enableDelivery &&
          !config.orderHistoryView.showOrderTracker &&
          order.status !== ORDER_STATUS.SHIPPED &&
          order.ext_data.instacart_order_url
        )
      }

      $scope.isProductDetailsModalDisabled = item => {
        return (
          item.isUnavailable ||
          !item.isReorderable ||
          (!eComLightService.inEComLightMode &&
            $scope.order.ext_data?.source_cart_type === CART_TYPES.CATERING) ||
          (eComLightService.inEComLightMode &&
            $scope.order.ext_data?.source_cart_type !== CART_TYPES.CATERING)
        )
      }

      /**
       * Returns true if the offer discount should be shown.
       *
       * @returns { boolean }
       */
      $scope.showOfferDiscount = () => {
        return (
          config.showOfferDiscountAndCreditsApplied ||
          parseInt(order.estimated_totals?.offer_total, 10) !== 0
        )
      }

      $scope.showInstacartLogo = showOnMobile => {
        return (
          $scope.mobile.isMobile === showOnMobile &&
          config.shoppingSelector.delivery.fulfilledByInstacart &&
          order?.fulfillment_type === FULFILLMENT_TYPES.DELIVERY
        )
      }

      $scope.showDeliveryInstructions = () => {
        return (
          order.fulfillment_type === 'delivery' &&
          config.newCheckoutFlow.checkoutPage.stepsOrder.find(
            step => step.name === 'orderDeliveryInstructions'
          )
        )
      }

      /**
       * Returns true if the credit applied should be shown.
       *
       * @returns { boolean }
       */
      $scope.showCreditsApplied = () => {
        return (
          config.showOfferDiscountAndCreditsApplied ||
          parseInt(order.estimated_totals?.credit_total, 10) !== 0
        )
      }

      $scope.showTipAndRatingButton = () => {
        return $scope.isShowTipAndRatingButton() && order.status === ORDER_STATUS.SHIPPED
      }

      $scope.shouldShowAddSelectedItemButton = () => {
        if (order.status === ORDER_STATUS.NEW) return false

        const currentShoppingMode = auth.getCurrentShoppingMode()
        const orderType = order.ext_data?.source_cart_type || order.fulfillment_type

        return currentShoppingMode === SHOPPING_MODES.CATERING
          ? orderType === CART_TYPES.CATERING
          : orderType !== CART_TYPES.CATERING
      }

      $scope.timeslotLabelFn = () =>
        timeslotUtils.getMainLabel($scope.order, { verboseLevel: 1, format: config.apiDateFormat })

      $scope.showInlineExternalProgressButton = () => {
        return (
          order.ext_data.instacart_order_url &&
          order.status === 'new' &&
          !config.orderHistoryView.disableViewOrderProgress
        )
      }

      $scope.showInlineExternalPickupButton = () => {
        return order.ext_data.pickup_status_url && order.status === 'new'
      }

      $scope.isShowTipAndRatingButton = () => {
        const { shoppingIntent } = $scope
        return (
          config.instacart.enableDelivery &&
          !config.orderHistoryView.disableTipAndRating &&
          shoppingIntent !== SHOPPING_MODES.INSTORE_LIST &&
          order.ext_data?.instacart_order_url
        )
      }

      $scope.isShowModifyButton = () => {
        return order.can_modify && !$scope.isOrderAddOnVisible()
      }

      $scope.isOrderAddOnVisible = () =>
        config.enableModifyOrderAddOn &&
        (order.can_add_items || order.additional_order_items.length > 0)

      $scope.isShowCancelButton = () => {
        return order.can_cancel
      }

      $scope.isShowOrderDetailButton = () => {
        return (
          order.ext_data.instacart_order_url && !config.orderHistoryView.disableViewOrderProgress
        )
      }

      $scope.isShowPickupDetailButton = () => {
        return (
          order.ext_data.pickup_status_url &&
          [FULFILLMENT_TYPES.PICKUP, FULFILLMENT_TYPES.CATERING].includes(order?.fulfillment_type)
        )
      }

      $scope.enableModifyButton = () =>
        order.ext_data.source_cart_type !== CART_TYPES.CATERING && !$rootScope.modifyingOrder

      $scope.enableCancelButton = () =>
        !$scope.cancellingOrder && order.ext_data.source_cart_type !== CART_TYPES.CATERING

      $scope.goToOrderInstacartLink = () => {
        instacartService.goToInstacartLink(order.ext_data.instacart_order_url)
      }

      $scope.goToPickupInstacartLink = () => {
        instacartService.goToInstacartLink(order.ext_data.pickup_status_url)
      }

      const { useItemEditFlow } = config.orderHistoryView
      $scope.orderTrackResources = {
        modifyButtonLang: useItemEditFlow ? lang.values.orderHistoryView.editItems : undefined,
        poweredByInstacart: utilService.loadAssetImage(POWERED_BY.LOGO),
        fulfillmentTypeLang:
          order.fulfillment_type === SHOPPING_MODES.PICKUP
            ? SHOPPING_MODES.PICKUP
            : SHOPPING_MODES.DELIVERY,
      }

      $scope.getOrderTotalsLines = () => {
        // Add appropriate order totals lines
        let setOrderTotalsLines = []

        const estimatedTotals = order.estimated_totals
        const finalTotals = order.final_totals

        const fees = [
          () => finalTotals?.fees,
          () => estimatedTotals?.fees,
          () => finalTotals,
          () => estimatedTotals,
        ]

        const addOrderTotalLineValue = (label, value, shouldAdd = true, additionalProps = {}) => {
          if (value && shouldAdd) {
            setOrderTotalsLines.push({ type: 'value', label, value, ...additionalProps })
          }
        }

        if (!finalTotals) {
          addOrderTotalLineValue(lang.values.subtotal, estimatedTotals.pre_discount_product_total)
          addOrderTotalLineValue(
            lang.values.creditsApplied,
            -estimatedTotals.credit_total,
            $scope.showCreditsApplied() && estimatedTotals.credit_total
          )
          addOrderTotalLineValue(
            lang.values.offerDiscount,
            -estimatedTotals.offer_total,
            $scope.showOfferDiscount() && estimatedTotals.offer_total
          )

          if ($scope.estimatedRewards.length) {
            $scope.estimatedRewards.forEach(reward => {
              setOrderTotalsLines.push({
                type: 'value',
                label: reward.label,
                value: reward.value,
              })
            })
          }

          addOrderTotalLineValue(lang.values.tip, estimatedTotals.tip_total)
          addOrderTotalLineValue(lang.values.subscriptionTotal, estimatedTotals.subscription_total)
          addOrderTotalLineValue(lang.values.tax, estimatedTotals.tax_total)

          setOrderTotalsLines = setOrderTotalsLines.concat(
            orderFeesService.formattedFees(fees, order.fulfillment_type)
          )

          addOrderTotalLineValue(lang.values.total, estimatedTotals.total)
        } else if (finalTotals) {
          addOrderTotalLineValue(lang.values.subtotal, finalTotals.pre_discount_product_total)
          addOrderTotalLineValue(
            lang.values.creditsApplied,
            -finalTotals.credit_total,
            finalTotals.credit_total
          )
          addOrderTotalLineValue(
            lang.values.offerDiscount,
            -finalTotals.offer_total,
            finalTotals.offer_total
          )
          addOrderTotalLineValue(
            lang.values.refundTotal,
            finalTotals.refund_total,
            finalTotals.refund_total && finalTotals.refund_total > 0
          )

          if ($scope.estimatedRewards.length) {
            $scope.estimatedRewards.forEach(reward => {
              setOrderTotalsLines.push({
                type: 'value',
                label: reward.label,
                value: reward.value,
              })
            })
          }

          addOrderTotalLineValue(lang.values.tip, finalTotals.tip_total)
          addOrderTotalLineValue(lang.values.subscriptionTotal, finalTotals.subscription_total)
          addOrderTotalLineValue(lang.values.tax, finalTotals.tax_total)

          setOrderTotalsLines = setOrderTotalsLines.concat(
            orderFeesService.formattedFees(fees, order.fulfillment_type)
          )

          addOrderTotalLineValue(lang.values.total, finalTotals.total)
        }

        // Ensures that a digset cycle is only triggered if the lines have changed.
        if (!_.isEqual(setOrderTotalsLines, $scope.orderTotalsLines)) {
          $scope.orderTotalsLines = setOrderTotalsLines
        }

        return $scope.orderTotalsLines
      }

      $scope.isUseItemEditFlow = () =>
        $scope.isShowModifyButton() && config.orderHistoryView.useItemEditFlow

      $scope.isUseRescheduleOrderButton = () =>
        $scope.isShowModifyButton() &&
        !$rootScope.modifyingOrder &&
        config.orderHistoryView.useRescheduleOrderButton

      $scope.jumpToCheckout = initialStep => {
        cartService
          .enterModifyState($scope.order, false, 'main.checkout.checkoutPage')
          .then(() => ($rootScope.isAppLoading = true))
          .then(() => $state.go('main.checkout.checkoutPage', { initialStep }))
      }

      $scope.isUseModifyOrderPaymentButton = () =>
        $scope.isShowModifyButton() &&
        !$rootScope.modifyingOrder &&
        !config.modifyOrder.readOnlyPayment &&
        config.orderHistoryView.useModifyOrderPaymentButton

      /**
       * Returns whether a given *order* was paid using a credit card online
       *
       * @param {object} order
       * @returns boolean
       */
      $scope.isPaidWithCreditCard = order =>
        order.payment_type === PAYMENT_TYPES.AUTH_CAPTURE &&
        order.payment_instruments &&
        order.payment_instruments.length > 0
    }
  )
  .run(($templateCache, paymentGateway) => {
    $templateCache.put('orderHistory.html', require('./views/orderHistory.html'))
    $templateCache.put('order.html', require('./views/order.html'))
    $templateCache.put('purchase.html', require('./views/purchase.html'))

    if (isNewFirstData(paymentGateway)) {
      $templateCache.put(
        'firstDataIntegrationv2.html',
        require('../../../optionalComponents/firstData/v2/firstDataIntegration.html')
      )
    } else {
      $templateCache.put(
        'firstDataIntegration.html',
        require('../../../optionalComponents/firstData/v1/firstDataIntegration.html')
      )
    }
  })
