/**
 * These constants are here for PCI Compliance
 * All iframe and js urls should be kept in this constants file which is bundled into the final
 * build. Please do not put any payment gateway url constants into a config - as we need to be able
 * to demonstrate that these urls cannot be changed post build!
 */
export const PROD = 'PROD'
export const NON_PROD = 'NON_PROD'

/**
 * Validates urls for providers that require a transactionSetupId
 * @param {string} url                  - The payment iframe/js url
 * @param {string} transactionSetupId   - The UUID (allowed characters a-f, A-F, 0-9 and -
 * @returns {string}                    - The complete url with transactionSetupId
 * @throws {Error}                      - Throws an error if the transactionSetupId is invalid
 */
const createValidatedPaymentUrl = (url, transactionSetupId) => {
  const isValidTransactionSetupId = !transactionSetupId.match(/[^a-fA-F0-9-]/)
  if (!isValidTransactionSetupId) {
    throw new Error(`Illegal transaction_setup_id passed: ${transactionSetupId}`)
  }
  return url + transactionSetupId
}

const AUTHORIZE_NET_TEST = 'test.authorize.net'
const AUTHORIZE_NET_LIVE = 'accept.authorize.net'
export const AUTHORIZE_NET = {
  FORM_ACTION_FN: ({ isProd, isEdit }) => {
    const url = isProd ? AUTHORIZE_NET_LIVE : AUTHORIZE_NET_TEST
    const mode = isEdit ? 'edit' : 'add'
    return `https://${url}/customer/${mode}Payment`
  },
}

