const VANTIV_TEST = 'certtransaction.hostedpayments.com'
const VANTIV_LIVE = 'transaction.hostedpayments.com'
export const VANTIV = {
  IFRAME_FN: ({ isProd, transactionSetupId }) => {
    const url = `https://${isProd ? VANTIV_LIVE : VANTIV_TEST}/?TransactionSetupID=`
    return createValidatedPaymentUrl(url, transactionSetupId)
  },
  URL:
    '/vantiv?HostedPaymentStatus&TransactionID&TransactionSetupID&ServicesID&ExpressResponseCode&ExpressResponseMessage&PaymentAccountID&LastFour&ValidationCode&CardLogo&args&CVVResponseCode&AVSResponseCode',
}

