// Allow including templates that may or may not exist

import angular from 'angular'

export default angular
  .module('IncludeIfComponentModule', [])
  .directive('includeIfExists', function ($compile, $templateCache, config) {
    return {
      restrict: 'A',
      scope: {
        includeIfExists: '=',
        ngModel: '=',
      },
      link(scope, el) {
        scope.config = config
        scope.parent = scope.$parent
        if ($templateCache.get(scope.includeIfExists)) {
          el.attr('ng-include', `'${scope.includeIfExists}'`)
          el.removeAttr('include-if-exists')
          $compile(el)(scope)
        }
      },
    }
  })
