import angular from 'angular'

export default angular
  .module('MobileAppInfoComponentModule', [])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.mobileAppInfo', {
      url: '/about-this-app',
      views: {
        'content@': {
          controller: 'MobileAppInfoController',
          template: require('./mobileAppInfo.html'),
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "mobileAppInfoController", webpackPrefetch: true */ './mobileAppInfoController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.aboutThisApp || 'About this app')
        },
      },
    })
  })
