import _ from 'lodash'
import angular from 'angular'
import { product } from '@instacart/enterprise-services'

import template from './relatedProduct.html'

export default angular
  .module('RelatedProductComponentModule', [])
  .directive('relatedProduct', function () {
    return {
      scope: {
        product: '=',
        location: '@',
        index: '<',
        condensedLayout: '<',
        horizontalLayout: '<?',
        isModal: '=?',
      },
      replace: true,
      template,
      controller(
        $document,
        $element,
        $sce,
        $scope,
        $timeout,
        auth,
        lang,
        cartService,
        config,
        configurableProductService,
        eComLightService,
        imageURLService,
        mobileService,
        productDetailsModal,
        productsService
      ) {
        $scope.config = config
        $scope.imageURLService = imageURLService
        $scope.isMobileApp = mobileService.isMobileApp()
        $scope.isMobile = mobileService.isMobile
        $scope.lang = lang
        $scope.auth = auth
        $scope.productDetailsModal = productDetailsModal
        $scope.showQuickAdd = false
        $scope.shoppingContext = auth.getContext().intent
        $scope.horizontalOos = $scope.horizontalLayout && !$scope.isMobile

        // Check if the product is in stock
        // Not to be confused with product the product availability status
        // The util checks against the IC catalog product ext_data.availability_score, not the Unata availability_status
        $scope.isProductInStock = product.utils.isProductAvailable($scope.product)

        // item not available online but might be available in store, still allow add to cart
        $scope.inStockOrInStore = $scope.isProductInStock || $scope.shoppingContext === 'instore'

        /*
         * HTML Popovers
         * This isn't an ideal way to do it, but I can't figure out how to make this DRY in Angular
         */
        $scope.outOfStockWarningPopover = $sce.trustAsHtml(
          `<span class="icon-warning add-to-cart__warning" aria-hidden="true"></span> ${lang.values.outOfStockDescription}`
        )

        /*
         * Add to cart overlay variant events
         */
        $scope.closeQuickAdd = (event, forceClose) => {
          const el = $element[0]

          // Detect where the element is clicked so that buttons inside don't close the overlay
          if ((el !== event?.target && !el.contains(event?.target)) || forceClose) {
            $scope.showQuickAdd = false
            $document.off('click', $scope.closeQuickAdd)

            // Force refresh
            $timeout(() => $scope.$apply())
          }
        }

        $scope.onQuickAddClick = () => {
          // Hack to get around binding issue
          $timeout(() => {
            $scope.showQuickAdd = true
            const el = document.getElementById(`add-to-cart-modify-add-button-${$scope.product.id}`)

            if (el) {
              // Hack to get around waiting for element to be visible
              // Focus the add to cart button so the user can continue to add to cart
              $timeout(() => {
                el.focus()
              }, 10)
            }

            $document.on('click', $scope.closeQuickAdd)
          })
        }

        // This method fires when there is change to cart so that we can update the Add To Cart button accordingly to
        // reflect any additions/removals.
        // NOTE: the below function and listeners are a slightly striped down version from productCell.js
        // TODO: Move this somewhere else to make it reuseable
        const updateForConfigurations = function () {
          if (
            $scope.product.product_config &&
            $scope.product.product_config.options &&
            $scope.product.product_config.options.length > 0 &&
            $scope.product.product_config.config_type === 'prep_style'
          ) {
            let selectedConfiguration = []

            $scope.aggregateQuantityCount = 0
            $scope.existingConfigurationCartItems = []
            $scope.existingConfigSingular = null

            // No default options available, show all options on Add To Cart popover
            $scope.showAllOptions = !configurableProductService.findDefault(
              $scope.product.product_config.options[0].items
            )

            // Checks all product's available configs and sees if they exist in cart
            _.each($scope.product.product_config.options[0].items, option => {
              selectedConfiguration = configurableProductService.formatConfigForCart(
                $scope.product,
                option
              )

              // Find configuration in cart and update quantity counts and aggregates in-cart configurations
              // To pass to addToCartButton popover
              const item = cartService.getItem($scope.product, selectedConfiguration)
              if (item && !_.find($scope.existingConfigurationCartItems, item)) {
                $scope.aggregateQuantityCount += item.quantity
                $scope.existingConfigurationCartItems.push(item)
                if ($scope.existingConfigurationCartItems.length === 1) {
                  $scope.aggregateQuantityCount = item.quantity
                  $scope.existingConfigSingular = item
                } else {
                  $scope.existingConfigSingular = null
                }
              }
            })
          }
        }

        if (!config.disableProductTileProductReco && !eComLightService.inEComLightMode) {
          const deregister = $scope.$on('productAdded', () => {
            updateForConfigurations()
          })
          $scope.$on('$destroy', () => {
            deregister()
          })
        }

        // Watchers to update configurable dropdown
        if (
          $scope.product.product_config &&
          $scope.product.product_config.options &&
          $scope.product.product_config.options.length > 0 &&
          $scope.product.product_config.config_type === 'prep_style'
        ) {
          const deregisterGroupWatcher = $scope.$watchGroup(
            ['cartService.lastUpdated', 'product'],
            updateForConfigurations
          )
          const deregisterCartServiceWatcher = $scope.$on('cartService', (event, method, item) => {
            const cartChange =
              ['add', 'remove'].includes(method) &&
              $scope.product &&
              $scope.product.id === item?.store_product?.id
            const cartCleared = method === 'clearConfirm'
            if (cartChange || cartCleared) {
              updateForConfigurations()
            }
          })

          if (cartService.cart) {
            updateForConfigurations()
          } else {
            cartService.fetch().then(updateForConfigurations)
          }

          $scope.$on('$destroy', () => {
            deregisterGroupWatcher()
            deregisterCartServiceWatcher()
          })
        }

        productsService.trackViewPort($element, $scope, $scope.product)
      },
    }
  })
