import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('SearchComponent', [])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.search', {
      url:
        '/search?search_term&tags&sort&page&category_id&brand_name&brand_names&offer_sources&group_display_name&search_is_autocomplete&allow_autocorrect',

      views: {
        'content@': {
          template: require('./views/search.html'),
          controller: 'SearchController',
        },
      },
      onEnter($state, $stateParams, tagsService) {
        // If any of the tags in the new state's params require registration, ensure the user either registers or is logged in
        if ($stateParams.tags && $stateParams.tags.length > 0) {
          const activeTags = _.isArray($stateParams.tags)
            ? angular.copy($stateParams.tags)
            : [$stateParams.tags]

          return tagsService.checkRegistrationRequired({
            activeTags,
            allTags: tagsService.tags(),
            success: () => {}, // User gets to continue with the route change
            failure: () => {
              // Did not login/register, refresh the page without the required filter/tags (removed in checkRegistrationRequired)
              $state.go('.', { tags: activeTags, page: null }, { reload: true })
            },
          })
        }
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "searchController", webpackPrefetch: true */ './searchController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title($stateParams, titleService) {
          return titleService.setTitle(
            `${langProvider.values.searchResults}: ${$stateParams.search_term}`
          )
        },
        fetchedRootCategories(catalogCategoriesService) {
          let categories = catalogCategoriesService.rootCategories()
          if (!categories) {
            categories = catalogCategoriesService.fetchCategories().then(() => {
              return catalogCategoriesService.rootCategories()
            })
          }
          return categories
        },
        searchCategories($stateParams, configConstants, Restangular) {
          if (configConstants.enableFacetedSearchFilters || configConstants.enableICSearch) {
            return null // Facets replaces searchCategories if enabled.
          }
          // Checks if client has a custom `offer_sources` for `on_sale` products
          if (
            $stateParams.tags &&
            $stateParams.tags.indexOf('on_sale') > -1 &&
            configConstants.onSaleOfferSources
          ) {
            $stateParams.offer_sources = configConstants.onSaleOfferSources
          }

          // Checks if client has a custom `offer_sources` for `has_coupon` products
          if (
            $stateParams.tags &&
            $stateParams.tags.indexOf('has_coupon') > -1 &&
            configConstants.hasCouponOfferSources
          ) {
            $stateParams.offer_sources = configConstants.hasCouponOfferSources
          }

          return Restangular.all('categories')
            .all('search')
            .getList({ keyword: $stateParams.search_term })
        },
      },
      params: {
        toggledFilterId: null,
      },
    })

    // For dotCom module search use
    $stateProvider.state('main.search.types', {
      url: '/types',
      views: {
        'content@': {
          template: require('./views/search.html'),
          controller: 'SearchPostsController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(/* webpackChunkName: "searchPostsController" */ './searchPostsController')
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
      },
      onEnter($state, searchService) {
        // Precautionary: if dotComSearch is disabled, then redirect to 404
        if (!searchService.isDotComEnabled()) {
          $state.go('main.404')
        }
      },
    })

    $stateProvider.state('main.search.recipes', {
      url: '/recipes',
      views: {
        'content@': {
          template: require('./views/search.html'),
          controller: 'SearchRecipesController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "searchRecipesController" */ './searchRecipesController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(
                langProvider.template(langProvider.values.defaultErrorMessageSuffix, { error: err })
              )
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.searchResults)
        },
        recipeTags: (tagsService, Restangular) => {
          return Restangular.all('recipes')
            .all('tags')
            .getList()
            .then(({ tags }) =>
              tags
                .map(tag => ({
                  ...tagsService.byVal(tag.tag_name),
                  name: tag.tag_display_name,
                }))
                // Filter out buggy tags with missing names and icons
                // TODO find a consistent way to inform devs about things like missing configs
                // either through proper logging or documentation
                .filter(v => !!v.name && !!v.icon)
            )
        },
        recipeCategoriesFetched: recipeCategoriesService => recipeCategoriesService.fetch(),
        // eslint-disable-next-line no-unused-vars
        recipeCategories: (recipeCategoriesService, recipeCategoriesFetched) => {
          // This currently does not return the actual root categories as
          // defined in the service as the recipe_categories/search endpoint
          // below does not return actual root categories. This may be specific
          // to Wegmans data only, but that's unknown currently
          // Context: https://github.com/instacart/unata-buffet/pull/6701/files#r327146390

          return recipeCategoriesService.categories
        },
        searchCategories: ($stateParams, recipeCategoriesService) => {
          return recipeCategoriesService.searchCategories({
            search_term: $stateParams.search_term,
            recipe_category_id: $stateParams.category_id,
            tags: $stateParams.tags,
          })
        },
      },
      onEnter($state, searchService) {
        // If recipes search is disabled, then redirect to 404
        if (!searchService.isRecipesEnabled()) {
          $state.go('main.404')
        }
      },
    })
  })
  .run($templateCache => {
    $templateCache.put('searchProducts.html', require('./views/searchProducts.html'))
    $templateCache.put('searchRecipes.html', require('./views/searchRecipes.html'))
    $templateCache.put('searchPosts.html', require('./views/searchPosts.html'))
    $templateCache.put(
      'pagination-modified-uibootstrap-desktop.html',
      require('../shared/pagination/pagination-modified-uibootstrap-desktop.html')
    )
    $templateCache.put(
      'pagination-modified-uibootstrap-mobile.html',
      require('../shared/pagination/pagination-modified-uibootstrap-mobile.html')
    )
  })
