import angular from 'angular'
import template from './collectionModal.html'
import CollectionModalControllerModule from './collectionModalController'

export default angular
  .module('CollectionModalServiceModule', [CollectionModalControllerModule.name])
  .service('collectionModal', function collectionModal($uibModal, lang, uibModalAriaLabelService) {
    const service = this

    service.open = function (endpoint, noticeAlert = null) {
      $uibModal.open({
        template,
        controller: 'CollectionModalController',
        resolve: {
          ariaLabel: () => {
            uibModalAriaLabelService.setLabel(lang.values.collections)
          },
          endpoint() {
            return endpoint
          },
          noticeAlert() {
            return noticeAlert
          },
        },
      })
    }
  })
