import angular from 'angular'

export default angular
  .module('InstacartCustomerLinkNotifyModule', [])
  .component('instacartCustomerLinkNotify', {
    bindings: {
      setStage: '<',
      closeModal: '<',
      skippable: '<',
      showLinkLater: '<',
    },
    controller(auth, instacartCustomerLinkService, lang, localStorageService) {
      const vm = this
      vm.auth = auth
      vm.lang = lang
      vm.isSending = false

      vm.sendCode = () => {
        vm.isSending = true
        instacartCustomerLinkService
          .sendLinkCode()
          .then(
            () => {
              vm.setStage('link')
            },
            response => {
              vm.error = response.data ? response.data.message : response.message
            }
          )
          .finally(() => {
            vm.isSending = false
          })
      }

      vm.checkLinkLater = () => {
        return vm.showLinkLater
      }

      vm.linkLater = () => {
        if (vm.skippable) {
          localStorageService.deprecatedSet('ic_linklater', true)
          vm.closeModal()
        }
      }
    },
    template: `
        <div class="subheader">{{::$ctrl.lang.values.instacart.deliveryByInstacart}}</div>
        <div class="instructions">
        <div class="subtitle">{{::$ctrl.lang.values.instacart.linkExistingInstacartAccount}}</div>
        <div>{{::$ctrl.lang.template($ctrl.lang.values.instacart.linkExistingInstacartAccountInstructions, {retailer: $ctrl.lang.values.clientName, emailAddress: $ctrl.auth.user.email})}}</div>
        <div ng-if="$ctrl.error" class="message error">
          <span class="icon-error" aria-hidden="true"></span>
          {{$ctrl.error}}
        </div>
        <div ng-if="$ctrl.message" class="message success">
          <span class="icon-checkmark-circle" aria-hidden="true"></span>
          {{$ctrl.message}}
        </div>
        </div>
        <div class="flexcontainer flexcoloumn">
            <button class="button" ng-click="$ctrl.sendCode()" ng-class="{disabled:($ctrl.isSending)}"><loading-dots show="$ctrl.isSending"></loading-dots>{{::$ctrl.lang.values.sendCodeNow}}</button>
            <button class="link" ng-if="$ctrl.checkLinkLater()" ng-click="$ctrl.linkLater()">{{::$ctrl.lang.values.linkAccountLater}}</button>
        </div>
    `,
  })
