import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import * as shoppingContextActions from '@app/services/shoppingContext/actions'
import * as shoppingContextSelectors from '@app/services/shoppingContext/selectors'

export default angular
  .module('shoppingContextServiceModule', [])
  .service('shoppingContextService', function ($ngRedux, auth, config, eComLightService) {
    const service = this

    const mapStateToService = state => ({
      selectedFulfillmentType: shoppingContextSelectors.currentFulfillmentType(state),
    })

    $ngRedux.connect(mapStateToService, {
      ...shoppingContextActions,
      // For compatibility with existing method name
      setSelectedFulfillmentType: shoppingContextActions.setFulfillmentType,
    })(service)

    service.getShoppingModeTitle = shoppingMode => {
      const shoppingModeConfig = eComLightService.inEComLightMode
        ? config.shoppingSelector[SHOPPING_MODES.CATERING]
        : config.shoppingSelector[shoppingMode || auth.getContext()?.intent]

      const { shortTitle, title } = shoppingModeConfig || {}

      return shortTitle || title
    }

    /**
     * Returns the user current fulfillment type
     *
     * @returns {string}
     */
    service.getIntentFromContext = () => {
      const { intent, shopping_mode } = auth.getContext() || {}

      if (
        config.eComLight?.enabled &&
        intent === config.eComLight.fulfillmentType &&
        shopping_mode === SHOPPING_MODES.CATERING
      ) {
        return SHOPPING_MODES.CATERING
      }

      return intent
    }
  })
