import angular from 'angular'
import ProfileMenu from '../nav/registrationDropdown/profileMenu'

export default angular
  .module('WordpressMobileSideNavItemsComponentModule', [ProfileMenu.name])
  .component('wordpressMobileSideNavItems', {
    /**
     * @param $scope
     * @param auth
     * @param {ClientConfig} config
     * @param mobileNavService
     * @param wordpressRestApiService
     */
    controller($scope, auth, config, mobileNavService, wordpressRestApiService) {
      this.auth = auth
      $scope.showProfileMenuIcons = config.sideNavProfileMenuShowIcons
      if (config.dotComModule.mobileNav) {
        wordpressRestApiService.getWordpressMenuItems().then(items => {
          this.mobileSideNavItems = items
        })
      }
      this.getMenuByAPI = api => {
        let targetMenu
        config.dotComModule.mobileNav.wordpressMenuItems.forEach(element => {
          if (element.api === api) {
            targetMenu = element
          }
        })
        return targetMenu
      }
      this.appendProfileAfter = api => {
        const targetMenu = this.getMenuByAPI(api)
        return targetMenu ? targetMenu.appendProfileAfter : false
      }
      this.hideMobileSideNav = () => {
        mobileNavService.hideSlideOutMenu()
      }
      /**
       Urls that we get from wordpress service are always absolute because they may be reused for CMS site navigation.
       Turn internal urls to relative to avoid leaving SPA on web and unresponsive menu for same page items on mobile app. External urls stay untouched.

       Examples:
       GIVEN config.env.webUrl = 'https://shop-uat.wegmans.com'
         WHEN url = 'https://shop-uat.wegmans.com' THEN internalUrlToRelative(url) === '/'
         WHEN url = 'https://shop-uat.wegmans.com/blah' THEN internalUrlToRelative(url) === '/blah'
         WHEN url = 'https://wegmans.com/blah' THEN internalUrlToRelative(url) === 'https://wegmans.com/blah'
       */
      this.internalUrlToRelative = url => {
        const domainWithProtocol = config.env.webUrl.replace(/\/$/, '')
        if (url.startsWith(domainWithProtocol)) {
          return url === domainWithProtocol ? '/' : url.replace(domainWithProtocol, '')
        }
        return url
      }
    },
    template: `<div class="wordpress-mobile-side-nav-items-container">
                <ul ng-repeat="menu in $ctrl.mobileSideNavItems" class="{{::menu.name}}">
                  <li ng-repeat="items in menu.items">
                    <a ng-if="::!items.state" href="{{::$ctrl.internalUrlToRelative(items.url)}}" ng-click="$ctrl.hideMobileSideNav()" data-test="side-nav-item-{{items.post_name}}">{{::items.title}}</a>
                  </li>
                  <li class="profile-menu-wrapper" ng-if="$ctrl.auth.user.status !== 'trial' && $ctrl.appendProfileAfter(menu.slug)">
                    <unata-profile-menu ng-class="{'profile-menu-with-icons': showProfileMenuIcons}" show-icons="showProfileMenuIcons" show-logout="false"></unata-profile-menu>
                  </li>
                </ul>
              </div>`,
  })
