import angular from 'angular'

export default angular
  .module('CompareCartComponentModule', [])
  .config(($ocLazyLoadProvider, $stateProvider, langProvider) => {
    $stateProvider.state('cartCompare', {
      url: '/cart_compare?initialStoreId&updatedStoreId&session-token&redirect',
      views: {
        'content@': {
          template: require('./views/compareCartState.html'),
          controller: 'CompareCartStateController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "compareCartStateController", webpackPrefetch: true */ './compareCartState.controller'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.cartCompare)
        },
      },
      onEnter: (auth, config) => {
        if (!auth.user.context || !auth.user.context.intent) {
          auth.setContext({ intent: config.defaultShoppingContext })
        }
      },
    })
  })
