import angular from 'angular'

export default angular
  .module('SnapEBTRemoveModalControllerModule', [])
  .controller(
    'SnapEBTRemoveModalController',
    function (
      $rootScope,
      $scope,
      $uibModalInstance,
      config,
      ebtCardService,
      lang,
      mobileService,
      userAnalytics
    ) {
      $scope.deactivateEbtFailed = false

      Object.assign($scope, { config, lang, mobileService })

      const handleRemoveError = () => {
        $scope.deactivateEbtFailed = true
      }

      const handleRemoveSuccess = () => {
        userAnalytics.logUserRemoveEbtCard()
        $scope.deactivateEbtFailed = false
        $uibModalInstance.close()
      }

      $scope.removeEbtCard = () => {
        return ebtCardService.removeEbtCard().then(handleRemoveSuccess).catch(handleRemoveError)
      }
    }
  )
