import angular from 'angular'

export default angular
  .module('SubscriptionComponentModule', [])
  .config(($stateProvider, configConstants, langProvider) => {
    $stateProvider.state('main.account.subscription', {
      url: '/subscription?subscription_code',
      views: {
        'content@main.account':
          configConstants.subscriptions?.enabled &&
          configConstants.subscriptions?.isProfilePageEnabled
            ? // Subscriptions V2 (React)
              {
                template: require('./subscription.react.html'),
                controller: 'SubscriptionReactController',
              }
            : // Subscriptions V1 (Ng)
              {
                template: require('./subscription.ng.html'),
                controller: 'SubscriptionNgController',
              },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          if (
            configConstants.subscriptions?.enabled &&
            configConstants.subscriptions?.isProfilePageEnabled
          ) {
            return import(
              /* webpackChunkName: "subscriptionReactController", webpackPrefetch: true */ './subscriptionController.react'
            )
              .then(mod => $ocLazyLoad.load(mod.default))
              .catch(err => {
                throw new Error(`Oops, something went wrong, ${err}`)
              })
          }

          return import(
            /* webpackChunkName: "subscriptionController", webpackPrefetch: true */ './subscriptionController.ng'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Oops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.subscription)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.subscription,
          },
        ],
      },
    })
  })
