import angular from 'angular'
import { subscriptions } from '@instacart/enterprise-services'
import promiseWrapper from '@app/utils/promiseWrapper'

export default angular
  .module('subscriptionCheckoutServiceModule', [])
  .service('subscriptionCheckoutService', function () {
    this.data = {}

    this.setData = newData => {
      this.data = { ...this.data, ...newData }
      return this.data
    }

    this.getData = () => {
      return this.data
    }

    this.getStoreSubscriptions = promiseWrapper(subscriptions.promiseActions.getStoreSubscriptions)
  })
