import _ from 'lodash'
import { auth as authService } from '@instacart/enterprise-services'
import angular from 'angular'
import { FacebookLogin } from '@capacitor-community/facebook-login'
import { AUTH_ACTIONS } from '@app/angular/constants/authActions'
import { SSO_FIELDS } from '@app/angular/constants/ssoFields'
import { SSO_PROVIDERS } from '@app/angular/constants/ssoProviders'
import { USER_ACTIONS } from '@app/angular/constants/events'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { ACTIONS } from '@app/angular/constants/recaptcha'
import handleExternalAuth from '@app/services/externalAuth/handleExternalAuth'
import { OneCartActions } from '@app/services/oneCart/types'
import isCapacitor from '@app/utils/isCapacitor'
import ApplePlugin from '@app/react/mobile/plugins/applePlugin'
import rxRedirectErrorModal from '@app/angular/components/rxRedirectError/rxRedirectErrorModal.html'
import { USER_STATUSES } from '@app/angular/constants/user'
import SocialSignOnLinkModalTemplate from './socialSignOnLinkModal.html'

export default angular
  .module('SocialSignOnComponentModule', [])
  .config($stateProvider => {
    $stateProvider.state('socialSignOn', {
      url:
        '/social-redirect/:provider?access_token&code&jwt&state&oauth_token&oauth_verifier&id_token&token&error&error_description&rx_session&is_rx_session_valid',
      onEnter(
        $location,
        $state,
        $stateParams,
        $uibModal,
        analytics,
        auth,
        config,
        errorModalService,
        importIcAccountService,
        rxUpSellService,
        lang,
        mobileService,
        socialSignOnService
      ) {
        const {
          provider = '',
          code,
          rx_session,
          is_rx_session_valid,
          error: errorCode,
          error_description: errorDescription,
        } = $stateParams

        // Redirect to home if user is already logged in
        if (auth.user?.status !== USER_STATUSES.TRIAL) {
          return $state.go('main.home')
        }

        const ssoKeys = SSO_FIELDS[provider.toUpperCase()]

        const redirectResponseUrl = mobileService.isMobileApp()
          ? config.env.webUrl + $location.url()
          : $location.$$absUrl

        if (rx_session || is_rx_session_valid) {
          analytics.logSegmentEvent('rx.sso_session', {
            rx_session,
            is_rx_session_valid,
          })
        }

        if (is_rx_session_valid === 'false') {
          analytics.logSegmentEvent('rx.sso_session_invalid', {
            success: false,
            rx_session,
          })
          return $uibModal.open({
            template: rxRedirectErrorModal,
            controller: [
              '$scope',
              '$state',
              ($scope, $state) => {
                $scope.continue = () => {
                  analytics.logSegmentEvent('rx.upsell_error_continue', {})
                  $state.go('main.home')
                }
              },
            ],
          })
        }

        if (is_rx_session_valid === 'true') {
          // User enters from RX flow should trigger rxUpSell dialog
          rxUpSellService.fromRxSSO(!!rx_session)
        }

        // If IC account import is enabled, do that instead of SSO flow
        if (config.icImportAccount && provider === SSO_PROVIDERS.INSTACART_LINK) {
          return importIcAccountService.handleSocialSignOnSuccess({
            code,
            redirectResponse: redirectResponseUrl,
          })
        }

        // Check that the Redirect URI has the expected parameters of a successful token, otherwise throw error modal
        if (ssoKeys && ssoKeys.every(key => key.split('|').some(k => !!$stateParams[k]))) {
          return socialSignOnService.handleSocialSignOnSuccess({
            identifier_polytype: provider,
            identifier_data: {
              redirect_response: redirectResponseUrl,
            },
          })
        }

        const authService = auth.getAuthService()

        if (authService && authService.handleError) {
          return authService.handleError({ errorCode, errorDescription })
        }

        // Generic error handling
        const errorMessage =
          (errorCode && lang.values.ssoErrors[errorCode]) || lang.values.defaultErrorMessage

        return errorModalService.open(errorMessage, lang.values.backToHome).then(() => {
          $state.go('main.home')
        })
      },
    })
  })

  // Service to power the social sign on linking flow.

  .service(
    'socialSignOnService',
    function socialSignOnService(
      $injector,
      $q,
      $rootScope,
      $state,
      $uibModal,
      $window,
      Restangular,
      anonymousIdService,
      auth,
      checkoutService,
      config,
      errorModalService,
      lang,
      localStorageService,
      mobileNativeHrefService,
      mobileService,
      oneCartService,
      redirectService,
      registrationService,
      userAnalytics
    ) {
      const service = this

      const genericSdkLogin = (loginFn, provider, errorKey) => {
        return $q((resolve, reject) => {
          loginFn(
            config.socialSignOn.mobileSdkParams[provider],
            data => {
              resolve(
                service.handleSocialSignOnSuccess({
                  identifier_polytype: provider,
                  includes_access_token: true,
                  identifier_data: data,
                })
              )
            },
            errorKey ? msg => reject(msg?.[errorKey]) : reject
          )
        })
      }

      const facebookSdkLogin = () => {
        if (isCapacitor()) {
          // Use native SDK plugin for FB login on Capacitor apps
          return FacebookLogin.login({
            permissions: config.socialSignOn.mobileSdkParams[SSO_PROVIDERS.FACEBOOK],
          })
            .then(loginResult => {
              const accessToken =
                loginResult && loginResult.accessToken && loginResult.accessToken.token
              if (accessToken) {
                // Format payload that buffet endpoint expects
                const formattedIdentifierData = Object.assign(loginResult, {
                  authResponse: { accessToken },
                })
                service.handleSocialSignOnSuccess({
                  identifier_polytype: SSO_PROVIDERS.FACEBOOK,
                  includes_access_token: true,
                  identifier_data: formattedIdentifierData,
                })
              }
            })
            .catch(error => error)
        }
        return genericSdkLogin(
          window.facebookConnectPlugin.login,
          SSO_PROVIDERS.FACEBOOK,
          'errorMessage'
        )
      }

      const googleSdkLogin = () =>
        genericSdkLogin(window.plugins.googleplus.login, SSO_PROVIDERS.GOOGLE)

      const appleSdkLogin = () => {
        if (isCapacitor()) {
          // Use native SDK plugin for Apple login on Capacitor apps
          return ApplePlugin.login().then(loginResult => {
            service.handleSocialSignOnSuccess({
              identifier_polytype: SSO_PROVIDERS.APPLE,
              includes_access_token: true,
              identifier_data: loginResult?.response,
            })
          })
          // No .catch() as we are returning the promise and there is other logic to catch the error
          // and display the error message in the login UI in sharedLogin.js
        }
        return genericSdkLogin(
          window.cordova.plugins.SignInWithApple.signin,
          SSO_PROVIDERS.APPLE,
          'localizedDescription'
        )
      }

      service.shouldUseNativeSdk = provider => {
        if (!(mobileService.isMobileApp() && config.socialSignOn.mobileSdkParams[provider])) {
          return false
        }
        switch (provider) {
          case SSO_PROVIDERS.FACEBOOK:
            return isCapacitor() ? !!FacebookLogin : !!window.facebookConnectPlugin
          case SSO_PROVIDERS.GOOGLE:
            return !!window.plugins.googleplus
          case SSO_PROVIDERS.APPLE:
            return isCapacitor() ? !!ApplePlugin : !!window.cordova.plugins.SignInWithApple
          default:
            return false
        }
      }

      service.handleSocialSignOn = provider => {
        if (service.shouldUseNativeSdk(provider)) {
          switch (provider) {
            case SSO_PROVIDERS.FACEBOOK:
              return facebookSdkLogin()
            case SSO_PROVIDERS.GOOGLE:
              return googleSdkLogin()
            case SSO_PROVIDERS.APPLE:
              return appleSdkLogin()
          }
        } else {
          return handleExternalAuth(provider)
        }
      }

      service.handleSocialSignOnSuccess = payload => {
        userAnalytics.logUserLogin()

        // POST to `auth/external` will let us know if this social account needs to be registered,
        // exists but needs link, or already linked and ready for login
        return authService.promiseActions
          .loginExternal(payload)
          .then(socialSignOnData => {
            if (socialSignOnData.action && service.socialSignOnFlows?.[socialSignOnData.action]) {
              return service.socialSignOnFlows[socialSignOnData.action](socialSignOnData)
            }
          })
          .catch(socialSignOnData => {
            const authService = auth.getAuthService()
            if (authService && authService.handleError) {
              return authService.handleError({ errorDescription: socialSignOnData?.message })
            }

            // Generic error handling
            // TODO: Handle specific error number if it's an issue with the oauth token not being in the session.
            // Please note, if a user closes the social sign-on account linking modal, socialSignOnData will be
            // undefined
            errorModalService
              .open(
                socialSignOnData?.message || lang.values.defaultErrorMessage,
                lang.values.backToHome
              )
              .finally(() => $state.go('main.home'))
            return Promise.reject()
          })
          .then(() => {
            const loginContext = localStorageService.deprecatedGet('loginContext', '')
            return redirectService.loadRedirectUrl({ state: 'main.home' }).then(() => {
              $rootScope.$broadcast('auth', USER_ACTIONS.POST_USER_EXTERNAL_LOGIN, loginContext)
              // redirectToPBICheckout relies on the checkout form existing (it cannot work on the social sign-on page)
              if (loginContext === LOGIN_CONTEXTS.REDIRECT_ONECART_CHECKOUT) {
                $rootScope.isAppLoading = true
                authService.api.auth.getOneCartCheckoutRedirect().then(resp => {
                  oneCartService.redirectToPBI(resp.location, resp.auth_token, {
                    action: OneCartActions.CHECKOUT,
                  })
                })
              }
              if (loginContext === LOGIN_CONTEXTS.REDIRECT_ONECART_ORDER_STATUS) {
                $rootScope.isAppLoading = true
                const icOrderId = localStorage.getItem('icOrderId')
                const icOrderQuery = localStorageService.deprecatedGet('icOrderQuery', {})
                authService.api.auth.getOneCartOrderRedirect(icOrderId, icOrderQuery).then(resp => {
                  oneCartService.redirectToPBI(resp.location, resp.auth_token, {
                    action: OneCartActions.ORDER_STATUS,
                    orderId: icOrderId,
                  })
                })
              }
              localStorageService.deprecatedRemove('loginContext')
              localStorage.removeItem('icOrderId')
              localStorageService.deprecatedRemove('icOrderQuery')
            })
          })
      }

      service.socialSignOnFlows = {
        registration: socialSignOnData =>
          // Use normal registration flow, but pass socialSignOnData object, which contains `user.social_accounts` for auto-linking
          registrationService.open('register', true, null, null, socialSignOnData),
        link: socialSignOnData => {
          return $uibModal.open({
            template: SocialSignOnLinkModalTemplate,
            controller: SocialSignOnLinkModalController,
            controllerAs: '$ctrl',
            keyboard: true,
            backdrop: 'static',
            resolve: {
              socialSignOnData: () => socialSignOnData,
            },
          }).result
        },
        login: auth.handlePostLogin,
        loyalty: () => registrationService.open(AUTH_ACTIONS.LOYALTY),
      }
      // Used in register flow to switch over to the link flow
      service.goToSocialLinkFlow = socialSignOnData => {
        // Set the social account's status to true before proceeding to the link flow
        _.each(socialSignOnData.user.social_accounts, account => {
          account.status = true
        })
        // return promise when link dialog close (resolve) or dismiss (reject)
        return service.socialSignOnFlows.link(socialSignOnData)
      }
    }
  )

function SocialSignOnLinkModalController(
  $scope,
  $state,
  $uibModalInstance,
  auth,
  config,
  mobileService,
  recaptchaService,
  registrationService,
  socialSignOnData,
  userAnalytics
) {
  this.config = config
  this.isMobile = mobileService.isMobile
  this.socialSignOnData = socialSignOnData

  this.linkFormModel = {
    email: socialSignOnData.social_email,
  }

  const socialAccountProviderToLink = socialSignOnData.identifier_polytype
  const socialAccountToLink = _.find(socialSignOnData.user.social_accounts, {
    provider: socialAccountProviderToLink,
  })
  this.linkFormModel.socialAccount = socialAccountToLink
  this.linkFormModel.socialAccount.status = true

  this.recaptcha = {
    enabled: recaptchaService.isEnabled(),
    token: null,
    action: ACTIONS.LOGIN,
    onSuccess: recaptchaToken => {
      $scope.$apply(() => {
        this.recaptcha.token = recaptchaToken
      })
    },
    onExpired: () => {
      $scope.$apply(() => {
        this.recaptcha.token = null
      })
    },
  }

  this.forgotPassword = () => {
    registrationService.open('forgot')
  }

  // Social sign on account linking after `auth/social` has determined social account already exists but has not been linked
  this.link = () => {
    delete this.linkFormModel.errorMessage
    this.linking = true

    auth
      .login(
        this.linkFormModel.email,
        this.linkFormModel.password,
        this.linkFormModel.socialAccount,
        LOGIN_CONTEXTS.SOCIAL_SIGN_ON_LINK_MODAL,
        this.recaptcha.enabled
          ? { token: this.recaptcha.token, siteKey: config.googleRecaptcha?.siteKey }
          : null
      )
      .then(() => {
        userAnalytics.logUserLoginAttempt({ success: true })
        $uibModalInstance.close({ linked: true })
      })
      .catch(response => {
        userAnalytics.logUserLoginAttempt({ success: false })
        this.linkFormModel.errorMessage = (response.data && response.data.message) || response
        this.recaptcha.token = null
        recaptchaService.resetLastCheckbox()
      })
      .finally(() => {
        this.linking = false
      })
  }
}
