import _ from 'lodash'
import angular from 'angular'
import template from './continuityProgress.html'

export default angular
  .module('ContinuityProgressComponentModule', [])
  .directive('continuityProgress', function () {
    return {
      scope: {
        offer: '=',
      },
      replace: true,
      template,
      controller($scope, cartService, config, lang, offerDetailsModalService, offersService) {
        $scope.config = config
        $scope.lang = lang
        $scope.offerDetailsModalService = offerDetailsModalService

        // We should use full offers for continuity - we need to PUT some info
        if ($scope.offer.offer) {
          offersService.getOffer($scope.offer.id).then(response => {
            $scope.offer = response
          })
        }

        const calculateContinuityOfferProgress = () => {
          // Work out the current stamp progress arrays for non-cart offers
          // ie) Progress [1,2,3] -- Remaining [4,5]
          if ($scope.offer.condition.type !== 'cart') {
            // This is a truncated offer being returned in the /cart API call
            if ($scope.offer.condition.max_value) {
              $scope.continuityStampProgress =
                $scope.offer.continuity.cdn_count > 0
                  ? _.range(1, $scope.offer.continuity.cdn_count + 1)
                  : []
              $scope.continuityStampProgressRemaining = _.range(
                $scope.continuityStampProgress.length + 1,
                Number($scope.offer.condition.max_value) + 1
              )
            } else {
              // Regular offer
              $scope.continuityStampProgress =
                $scope.offer.continuity.cdn_count > 0
                  ? _.range(1, $scope.offer.continuity.cdn_count + 1)
                  : []
              $scope.continuityStampProgressRemaining = _.range(
                $scope.continuityStampProgress.length + 1,
                Number($scope.offer.condition[$scope.offer.condition.type].quantity) + 1
              )
            }

            // Some 3rd parties will prevent the quantity from being set until after the offer is activated - check for updates to this value and update continuityStampProgress
            $scope.$watchCollection('offer', (newVal, oldVal) => {
              if (newVal !== oldVal) {
                $scope.continuityStampProgressRemaining = _.range(
                  $scope.continuityStampProgress.length + 1,
                  Number($scope.offer.condition[$scope.offer.condition.type].quantity) + 1
                )
              }
            })

            // Max out at 10 stamps. Essentially a design choice right now, if a
            // client wants more we'll need a different tile design.
            if ($scope.continuityStampProgress.length > 10) {
              $scope.continuityStampProgress = $scope.continuityStampProgress.slice(0, 10)
            }

            if ($scope.continuityStampProgressRemaining.length > 10) {
              $scope.continuityStampProgressRemaining = $scope.continuityStampProgressRemaining.slice(
                0,
                10
              )
            }
          }

          // Work out current cart $ progress bar for cart offers
          // ie) $430 of $500
          if ($scope.offer.condition.type === 'cart') {
            // This is a truncated offer being returned in the /cart API call
            if ($scope.offer.condition.max_value) {
              $scope.continuityCartProgressMax = $scope.offer.condition.max_value
            } else {
              // Regular offer
              $scope.continuityCartProgressMax = $scope.offer.condition.cart.min_spend
            }

            $scope.continuityProgressBarWidth = `${Math.min(
              Math.max(
                Number(
                  ($scope.offer.continuity.cdn_count / $scope.continuityCartProgressMax) * 100
                ),
                0
              ),
              100
            )}%`
          }
        }

        // Calculate the progress of active offers
        if ($scope.offer.continuity && $scope.offer.continuity.offer_activated) {
          calculateContinuityOfferProgress()
        }

        // Start a continuity offer by sending `offer_activated = true`
        $scope.startContinuityOffer = (event, offer) => {
          // Don't trigger the default offer cell modal opening
          if (event) {
            event.stopPropagation()
          }

          offer.offer_activated = true
          offer.continuity.offer_activated = true

          offer.put().then(response => {
            $scope.offer = response
            calculateContinuityOfferProgress()
            cartService.fetch()
          })
        }
      },
    }
  })
