import angular from 'angular'

export default angular
  .module('CheckoutManualUpsellControllerModule', [])
  .controller(
    'CheckoutManualUpsellController',
    function (
      $scope,
      $state,
      $window,
      analytics,
      auth,
      cartService,
      config,
      mobileService,
      reservationService,
      upsellProducts
    ) {
      Object.assign($scope, {
        cartService,
        config,
        mobileService,
        reservationService,
        $state,
        upsellProducts,
      })

      if (config.newCheckoutFlow.checkoutPage.enabled) {
        $scope.nextState = 'main.checkout.checkoutPage'
      } else if (auth.shouldUseGigyaAddressEdit()) {
        $scope.nextState = 'main.checkout.reservation.confirmation'
      } else {
        $scope.nextState = 'main.checkout.reservation.address'
      }

      $scope.moreThanOneRow = () =>
        upsellProducts.length > 4 ||
        (upsellProducts.length === 3 && $window.innerWidth < 680) ||
        (upsellProducts.length === 4 && $window.innerWidth < 912)
    }
  )
