import angular from 'angular'
import { EXPIRATION_DATE_FORMAT } from '@app/angular/constants/express'

import template from './forthStep.html'

class ExpressCancelForthStep {
  constructor(config, express, lang) {
    Object.assign(this, {
      config,
      express,
      lang,
    })
  }

  getTitle() {
    const { membershipTitle, trialTitle } = this.config.icExpress.cancellation.forthStep

    return this.lang.templateAsString(this.express.isTrial() ? trialTitle : membershipTitle, {
      express: this.config.icExpress.shortName,
    })
  }

  getBody() {
    const {
      refundEligibleText,
      refundIneligibleText,
      trialText,
    } = this.config.icExpress.cancellation.forthStep

    if (this.express.isTrial()) {
      return this.lang.templateAsString(trialText, {
        date: this.express.getMembershipEndDate().format(EXPIRATION_DATE_FORMAT),
      })
    }

    return this.lang.templateAsString(
      this.express.isRefundEligible() ? refundEligibleText : refundIneligibleText,
      { express: this.config.icExpress.shortName }
    )
  }
}

export default angular
  .module('IcExpressCancelForthStepComponentModule', [])
  .component('expressCancelForthStep', {
    template,
    controller: ExpressCancelForthStep,
  })
