import angular from 'angular'
import slugify from 'slugify'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import template from './productDetailsModal.html'
import ProductDetailsControllerModule from './productDetailsModalController.js'

export default angular
  .module('ProductDetailsModalComponentModule', [ProductDetailsControllerModule.name])
  .service(
    'productDetailsModal',
    function productDetailsModal($state, $uibModal, legacyModalService, uibModalAriaLabelService) {
      const service = this

      service.open = (productId, modalOptions, placement = {}, noticeAlert = null) => {
        legacyModalService.closeOpenModals()
        $uibModal.open({
          template,
          controller: 'ProductDetailsModalController',
          resolve: {
            ariaLabel: () => {
              uibModalAriaLabelService.setLabel('Product Details')
            },
            productId: () => productId,
            modalOptions: () => modalOptions,
            noticeAlert: () => noticeAlert,
            placement: () => placement,
          },
        })
      }

      eventEmitter.on(
        EVENT_TYPES.LINK_TO_PRODUCT_DETAIL,
        (product, openAsModal, location, placement) => {
          if (openAsModal) {
            service.open(
              product.id,
              { location },
              {
                source: product?.sponsoredBy,
                ...placement,
              }
            )
          } else {
            $state.go('main.product', {
              id: product.id,
              placement,
              name: slugify(product.name, { lower: true }),
            })
          }
        }
      )

      eventEmitter.on(EVENT_TYPES.LINK_TO_USERLIST_DETAIL, (product, userList) => {
        $state.go('list', { list_id: userList.id })
      })
    }
  )
