import _ from 'lodash'
import angular from 'angular'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import cookingModeModalTemplate from './cookingModeModal.html'
import shopThisRecipeModalTemplate from './shopThisRecipeModal.html'

export default angular
  .module('RecipeDetailsControllerModule', [])
  .controller(
    'RecipeDetailsController',
    function RecipeDetailsController(
      $rootScope,
      $scope,
      $state,
      $timeout,
      $uibModal,
      addressList,
      analytics,
      auth,
      config,
      imageURLService,
      lang,
      mobileService,
      recipe,
      registrationService,
      savedRecipes,
      savedRecipesService,
      tagsService,
      titleService,
      uibModalAriaLabelService,
      utilService
    ) {
      const vm = this
      vm.config = config
      vm.imageURLService = imageURLService
      vm.lang = lang
      vm.recipe = recipe
      vm.mobile = mobileService
      vm.tagsService = tagsService

      const recipeTags = vm.recipe.tags && vm.recipe.tags.tags ? vm.recipe.tags.tags : []

      // combine tags/values with matching configuration tags to simplify retrieving things like color
      // that we need in the template
      vm.recipeFullTags = recipeTags.map(tag => {
        return { ...tagsService.byVal(tag.tag_name), ...tag }
      })

      const initSaveStatus = _.some(savedRecipes, savedRecipe => {
        return savedRecipe.id === vm.recipe.id
      })

      const promptCloseDelayTime = 500

      // We need to nest a model object in vm to update the view for $on broadcast
      // from cart service and other adjacent scopes. #JustAngularThings
      vm.model = {
        showSavePrompt: false,
        isSaveButtonInView: true,
        savedCount: recipe.saved_count,
        isSaved: initSaveStatus,
      }

      if ($state.current.data) {
        // setPageTitleAndBreadcrumbs is set in recipesRoutes.js
        // (it will only be true if the user has landed on the recipeDetails page, and not just opened the modal in mobile)
        if ($state.current.data.setPageTitleAndBreadcrumbs) {
          // Set Page Title
          titleService.setTitle(vm.recipe.name)

          // Set Breadcrumbs
          const { recipesHomeState } = $state.current.data
          recipesHomeState.label = recipesHomeState.name // breadcrumbs expects 'label', not 'name'

          // Recipes can belong to multiple category trees. In some user flows, a user traverses a single category tree (read: breadcrumbs) and lands on a particular recipe.
          // For example, the following user path: shop -> recipes -> recipes by course -> Breakfast & Brunch course -> Sweet Potato & Apple Hash recipe
          // In those cases, we want to only display the `By Course / Breakfast & Brunch` category tree on the recipe page and not any others that the recipe may belong to.
          // In order to do that, we can be passed a categoryIds array which contains the ids of the categories to display on the recipe page.
          // Paths where user doesn't traverse a category tree to get to the recipe (for example, search), would continue to display the all of the category trees that the recipe belongs to.
          const filteredCategories = $state.params.categoryIds
            ? vm.recipe.categories.filter(category => {
                return $state.params.categoryIds.includes(category.id)
              })
            : vm.recipe.categories

          vm.breadcrumbs = [
            recipesHomeState,
            ...filteredCategories.map(category => ({
              label: category.name,
              state: 'main.shop.recipes.category',
              params: { recipe_category_id: category.id },
            })),
            { label: vm.recipe.name, active: true },
          ]
        }
      }

      $scope.mobileActiveTab = 'ingredients' // initialize active tab

      $scope.activateTab = tab => {
        $scope.mobileActiveTab = tab
      }

      vm.openCookingModeModal = () => {
        $uibModal.open({
          template: cookingModeModalTemplate,
          controller: function cookingModalController(recipe) {
            const vm = this

            vm.recipe = recipe

            vm.mobileActiveTab = $scope.mobileActiveTab

            vm.directions = {
              currentStep: 1, // initialize directions step
              totalSteps: recipe.directions.length,
              stepsChecked: recipe.directions.map(() => false), // array where index is true if checked, initialized to false
            }
            vm.directionsPreviousStep = () => {
              // eslint-disable-next-line no-plusplus
              vm.directions.currentStep--
              document.getElementById('step-counter-heading').focus()
            }
            vm.directionsNextStep = () => {
              // eslint-disable-next-line no-plusplus
              vm.directions.currentStep++
              document.getElementById('step-counter-heading').focus()
            }

            $scope.activateTab('directions')
          },
          controllerAs: 'cookingModeModal',
          resolve: {
            ariaLabel: () => {
              uibModalAriaLabelService.setLabel(lang.values.cookingMode)
            },
            recipe: () => vm.recipe,
          },
        })
      }

      vm.recipeHasProducts =
        (vm.recipe.products.fresh && vm.recipe.products.fresh.length > 0) ||
        (vm.recipe.products.mealkitOptions && vm.recipe.products.mealkitOptions.length > 0) ||
        (vm.recipe.products.pantry && vm.recipe.products.pantry.length > 0) ||
        (vm.recipe.products.other && vm.recipe.products.other.length > 0)

      if (vm.recipeHasProducts) {
        vm.openShopThisRecipeModal = () => {
          $uibModal.open({
            template: shopThisRecipeModalTemplate,
            controller: function shopThisRecipeModalController(products) {
              const vm = this
              vm.products = products
              vm.mobileActiveTab = $scope.mobileActiveTab
            },
            controllerAs: 'shopThisRecipeModal',
            resolve: {
              products: () => vm.recipe.products,
            },
          })
        }
      }

      // Save Recipe functionality

      vm.toggleSaveRecipe = () => {
        savedRecipesService.requireLogin().then(() => {
          if (vm.model.isSaved) {
            savedRecipesService
              .removeSavedRecipe(vm.recipe.id, vm.recipe, $state.params.index)
              .then(() => {
                vm.model.isSaved = false
                // eslint-disable-next-line no-plusplus
                vm.model.savedCount--
              })
          } else {
            savedRecipesService
              .saveRecipe(vm.recipe.id, vm.recipe, $state.params.index)
              .then(() => {
                vm.model.isSaved = true
                // eslint-disable-next-line no-plusplus
                vm.model.savedCount++

                $timeout(() => {
                  vm.model.showSavePrompt = false
                }, promptCloseDelayTime)
              })
          }
        })
      }

      vm.closeSavePrompt = () => {
        vm.model.showSavePrompt = false
        vm.model.userClosedSavePrompt = true
      }

      vm.reviewsUserData = {
        id: auth.user.id,
        name: auth.getUserFullName(),
        location: addressList.getAddressRegionString(addressList.getDefault()),
        isTrial: auth.user.status === 'trial',
      }

      vm.openLogin = () => {
        registrationService.open(
          'login',
          null,
          null,
          null,
          null,
          null,
          LOGIN_CONTEXTS.RECIPE_USER_REVIEW
        )
      }

      $scope.openAndScrollToTab = tab => {
        $scope.activateTab(tab)

        const el = document.querySelector('#recipe-details-extras')

        if (!el) {
          return
        }

        utilService.scrollIntoView(el)
      }

      analytics.logSegmentEvent('recipe.view_recipe', {
        recipe_details: analytics.getRecipeAnalytics(recipe, $state.params.index),
      })

      analytics.logRecipeDetailsView(recipe)

      $rootScope.$on('cartService', (event, action, item) => {
        if (
          action === 'add' &&
          !vm.model.showSavePrompt &&
          !vm.model.userClosedSavePrompt &&
          item.quantity !== 0
        ) {
          vm.model.showSavePrompt = true
        }
      })
    }
  )
  .run($templateCache => {
    $templateCache.put('recipeDetails.html', require('./recipeDetails.html'))
  })
