import angular from 'angular'

export default angular
  .module('UnataNavCartModule', [])
  .component('unataNavCart', {
    controller($rootScope, $scope, $state, auth, mobileService, cartService, sideCartService) {
      this.isMobile = mobileService.isMobile
      this.goToNextStep = sideCartService.goToNextStep
    },
    template: `
      <react-cart-button
        ng-if=":: !$ctrl.isMobile"
        analytics-click="['nav cart checkout button', 'click']"
        on-click="$ctrl.goToNextStep"
      >
      </react-cart-button>

      <react-cart-icon-button
        ng-if=":: $ctrl.isMobile"
        on-click="$ctrl.goToNextStep"
      ></react-cart-icon-button>
    `,
  })
  .component('unataCartTotalLabel', {
    controller(cartService) {
      this.isCartEmpty = () => !cartService.cart
      this.getCartTotal = () =>
        !this.isCartEmpty() ? cartService.getCartTotalForCurrentType() : null
      this.isCartSyncing = () => cartService.syncing
    },
    bindings: {
      labelId: '@',
    },
    template: `
      <span id="{{::$ctrl.labelId || 'cart-button-total-amount'}}" class="total" ng-if="!$ctrl.isCartSyncing()">{{$ctrl.getCartTotal() | currency}}</span>
      <loading-dots show="$ctrl.isCartSyncing() || $ctrl.isCartEmpty()"></loading-dots>`,
  })
