import angular from 'angular'
import { config as configService } from '@instacart/enterprise-services'
import { mergeConfigs } from '@instacart/enterprise-config-utils'
import { store } from '@app/services/store'
import { fetchConfig } from '@app/services/config/slice'

// This service allows you to fetch and override the client runtime
// configuration via remote fetch where needed
// (intended as our stopgap A/B solution)
export default angular
  .module('configServiceModule', [])
  .service('config', function configurationService($rootScope) {
    // WARNING: THIS IS A COMPLETE HACK TO WORKAROUND HOW WE READ CONFIGS

    // Configs arriving from Redux are frozen. However, the angular app
    // sometimes modifies some properties. This function will create an
    // "unfrozen" copy of the frozen Redux Config object
    const unfrozenCopy = obj => JSON.parse(JSON.stringify(obj))

    // storeConfig is stored so that we can compare changes later
    let storeConfig = configService.selectors.getConfig()

    // First, we make a deep copy of the current config so that it can later be
    // mutated when or if new configs are set or fetched
    const config = unfrozenCopy(storeConfig)

    // This function does a deep-merge on the above config instance and the
    // latest configs in the service. A deep-merge is required due to that
    // the original configs have already been returned by this function
    const updateConfig = () => {
      const newConfig = unfrozenCopy(configService.selectors.getConfig())
      mergeConfigs(config, newConfig)
    }

    store.subscribe(() => {
      const latestConfig = configService.selectors.getConfig()

      // If config didn't change, no need to do anything
      if (latestConfig === storeConfig) {
        return
      }

      // If config did change in the main store,
      // we update the local config as well as the reference
      updateConfig()
      storeConfig = latestConfig
    })

    // Re-fetch the configs when user becomes logged in. We do this to ensure
    // any A/B test custom configs can be loaded for logged in users
    $rootScope.$on('auth', (_event, action) => {
      if (action === 'userLoggedIn') {
        store.dispatch(fetchConfig()).then(updateConfig)
      }
    })

    // This function will return the original config immediately. To update
    // the config we must mutate the original object
    return config
  })
