import angular from 'angular'
import template from './productComments.html'

export default angular
  .module('ProductCommentsComponentModule', [])
  .directive('productComments', function () {
    return {
      scope: {
        item: '=',
        productCommentCallback: '&',
        disableInput: '<?',
      },
      template,
      controller($scope, cartService, config, lang, mobileService, oneCartService, utilService) {
        Object.assign($scope, {
          lang,
          config,
          cartService,
          util: utilService,
          mobile: mobileService,
        })

        $scope.finishEditing = function () {
          $scope.$emit('finishedEditingNotes')
        }

        $scope.disableProductComments = () => {
          return (
            $scope.disableInput ||
            $scope.config.disableInput.item_comments ||
            $scope.item.product_config
          )
        }

        $scope.hideProductComments = () => !!$scope.item.product_config
      },
    }
  })
