import _ from 'lodash'
import angular from 'angular'
import tagHelper from '@app/angular/reactComponents/checkbox/tagHelper'

export default angular
  .module('RecipeCategoriesService', [])

  .service(
    'recipeCategoriesService',
    function recipeCategoriesService($q, Restangular, auth, tagsService) {
      const service = this

      service.categories = []
      service.tags = []

      const buildTag = tag => {
        const configTags = tagsService.tags()
        const matchedTag = configTags.find(ctag => ctag.id === tag.tag_name)
        const tagFixes = tagHelper(matchedTag)
        return { ...matchedTag, ...tagFixes }
      }

      service.fetch = (refresh = false) => {
        const deferred = $q.defer()

        if (!refresh && service.categories.length && service.tags.length) {
          // Categories have been loaded, send them back without calling API
          deferred.resolve(service)
        } else {
          return $q
            .all({
              categories: Restangular.all('recipe_categories').getList({
                store_id: auth.user.store.id,
              }),
              recipeTags: Restangular.one('recipes').one('tags').get(),
            })
            .then(({ categories, recipeTags }) => {
              service.categories = categories.plain()
              service.tags = recipeTags.tags
                .map(tag => buildTag(tag))
                .filter(tag => Object.keys(tag).length)
              // Mutate categories objects in array by adding tree data props (children, department, etc.)
              _.each(service.categories, category => {
                category.parents = service.getParents(category)
                category.department = category.parents[0]
                category.children = service.getChildren(category)
                category.state = 'main.shop.recipes.category'
                category.params = { recipe_category_id: category.id }

                // If a category is missing an image, use their parent category's corresponding image
                if (category.images) {
                  // eslint-disable-next-line no-restricted-syntax
                  for (const image in category.images) {
                    if (!category.images[image] && category.parents) {
                      const parentCategoryWithImage = _.find(
                        category.parents,
                        parentCategory => parentCategory.images && parentCategory.images[image]
                      )
                      if (parentCategoryWithImage) {
                        category.images[image] = parentCategoryWithImage.images[image]
                      }
                    }
                  }
                }
              })
              deferred.resolve(service)
              return service
            })
        }

        return deferred.promise
      }

      service.searchCategories = ({ search_term, recipe_category_id, tags }) => {
        return Restangular.all('recipe_categories').all('search').getList({
          search_term,
          recipe_category_id,
          tags,
        })
      }

      service.rootCategories = () => service.categories.filter(category => !category.parent_id)

      service.getCategoryFromId = id => {
        const categoryFromId =
          // eslint-disable-next-line eqeqeq
          id && _.find(service.categories, serviceCategory => serviceCategory.id == id)
        return (
          categoryFromId || {
            level: null,
            // tags: service.tags,
            children: service.rootCategories(),
          }
        )
      }

      service.getParents = category => {
        const parent =
          category &&
          _.find(service.categories, potentialParent => potentialParent.id === category.parent_id)
        return parent ? [...service.getParents(parent), parent] : []
      }

      service.getSiblings = category =>
        category
          ? service.categories.filter(
              serviceCategory => serviceCategory.parent_id === category.parent_id
            )
          : []

      service.getChildren = category =>
        category
          ? service.categories.filter(serviceCategory => serviceCategory.parent_id === category.id)
          : []
    }
  )
