import angular from 'angular'

import { SHOPPING_MODES } from '@app/angular/constants/context'
import appendMetaTagDescription from '@app/utils/appendMetaTagDescription'

import { appendProductStruturedData } from '@app/utils/appendStructuredData'
import { deleteJsonLd } from '@app/utils/jsonLd'
import template from './productDetails.html'
import ProductDetailsControllerModule from './productDetailsController'
import ConfigurableProductDirectiveModule from './configurableProduct/configurableProduct.directive'
import ConfigurableProductServiceModule from './configurableProduct/configurableProduct.service'
import ConfigurableProductDropdownModule from './configurableProduct/configurableProductDropdown'

export default angular
  .module('ProductDetailsComponentModule', [
    ProductDetailsControllerModule.name,
    ConfigurableProductDirectiveModule.name,
    ConfigurableProductServiceModule.name,
    ConfigurableProductDropdownModule.name,
  ])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.product', {
      url: '/product/{id}/{name}?tab',
      views: {
        'content@': {
          template,
          controller: 'ProductDetailsController',
        },
      },
      params: {
        // If a name/slug is null for any reason, simply ignore it rather than 404
        name: { squash: true, value: null },
        placement: {},
      },
      resolve: {
        productId: $stateParams => {
          return $stateParams.id
        },
        placement: $stateParams => $stateParams.placement,
        product: ($state, config, productId, Restangular) => {
          return Restangular.one('store_products', productId)
            .get({ require_storeproduct: !config.enableUnavailableProductPDP })
            .catch(response => {
              if (response.status === 404) {
                $state.go('main.404')
              }
            })
        },

        tree: (auth, config, eComLightService, product) => {
          const ecomLightCategoryID = config.eComLight?.homeRedirect?.params?.category_id

          /**
           * If the retailer has ecomLight enabled and the user is accessing a subcategory of ecomlight,
           * set ecomlight mode to true
           */
          if (
            config.eComLight?.enabled &&
            config.isDedicatedCateringExperienceSupported &&
            ecomLightCategoryID &&
            !eComLightService.inEComLightMode &&
            product.categories.find(category => category.id === ecomLightCategoryID)
          ) {
            eComLightService.setMode(true)
            return auth.setIntent(SHOPPING_MODES.CATERING)
          }
        },
      },
      onEnter: product => {
        appendProductStruturedData(product, langProvider.values.clientName)
        appendMetaTagDescription(product.description)
      },
      onExit: () => {
        deleteJsonLd()
      },
    })
  })
