/* eslint-disable max-lines */

import _ from 'lodash'

import angular from 'angular'
import debounce from 'lodash/debounce'

import { Angular as AngularIntegration } from '@sentry/integrations'

import dropdown from 'angular-ui-bootstrap/src/dropdown'
import modal from 'angular-ui-bootstrap/src/modal'
import pagination from 'angular-ui-bootstrap/src/pagination'
import typeahead from 'angular-ui-bootstrap/src/typeahead'
import tooltip from 'angular-ui-bootstrap/src/tooltip'
import popover from 'angular-ui-bootstrap/src/popover'

import restangular from '@app/vendors/restangular'
import ngRedux from 'ng-redux'
import ngAria from 'angular-aria'
import ngTouch from 'angular-touch'
import uiMask from 'angular-ui-mask'
import uiRouter from '@uirouter/angularjs'
import ngSanitize from 'angular-sanitize'
import ocLazyLoad from 'oclazyload'

import AdditionalComponents from 'ClientPath/config/components.js'
import { config as configService, order } from '@instacart/enterprise-services'
import preloadCheckoutPage from '@app/utils/preloadCheckoutPage'
import '@app/vendors/circles/circles.js'

import { store } from '@app/services/store'
import { getRuntimeConfig } from '@app/rootConfig'

import { shouldUseHashHistory, hashTypeValue } from '@app/react/history'
import { fetchConfig } from '@app/services/config/slice'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import { isReactDemo } from '@app/utils/isReactDemo'
import { fetchUserLists } from '@app/services/userList/thunks'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { fixCapacitorCookies } from '@app/utils/mobileApp/fixCapacitorCookies'
import { LOGIN_CONTEXTS } from './constants/loginContexts'
import Components from './components'
import Services from './services'
import OptionalComponents from './optionalComponents'
import ReactComponents from './reactComponents'

import { locations as StatesAndProvinces } from './constants/geo_locations'
import { DEVICE_TYPES } from './services/modules/mobile'
import { AUTH_ACTIONS } from './constants/authActions'

import LazyLoadingService from './services/modules/lazyLoading.service'

fixCapacitorCookies()

const clientConfigModule = angular
  .module('clientConfigModule', [])
  .value('values', StatesAndProvinces)
  .constant('theme', getRuntimeConfig().theme)

export const setupAngularApp = isBarebone => {
  // eslint-disable-next-line angular/module-getter
  clientConfigModule.constant('configConstants', configService.selectors.getConfig())

  angular
    .module('unata', [
      AngularIntegration.moduleName,
      clientConfigModule.name,
      ngAria,
      ngRedux,
      ngTouch,
      uiMask,
      restangular,
      uiRouter,
      ngSanitize,
      ocLazyLoad,
      dropdown,
      modal,
      pagination,
      typeahead,
      tooltip,
      popover,
      Services.name,
      Components.name,
      LazyLoadingService.name,
      OptionalComponents.name,
      ReactComponents.name,
      AdditionalComponents.name,
    ])
    .constant('isBarebone', isBarebone)
    .config(
      (
        $ariaProvider,
        $compileProvider,
        $locationProvider,
        $ngReduxProvider,
        $ocLazyLoadProvider,
        $sceDelegateProvider,
        $urlRouterProvider,
        configConstants
      ) => {
        $locationProvider.hashPrefix(hashTypeValue)
        $locationProvider.html5Mode(!shouldUseHashHistory())

        // Whitelist the url for the header/footer

        $sceDelegateProvider.resourceUrlWhitelist([
          // Allow same origin resource loads.
          'self',
          // Allow loading of .com Module header/footer
          configConstants.dotComModule.headerFooterLocation,
          `${configConstants.dotComModule.url}/wp-json/**/**/**`,
          `${configConstants.dotComModule.url}/wp-json/**/**/**/**`,
        ])

        // Disable angular debugging info for prod environments
        if (['prod', 'production'].includes(process.env.NODE_ENV)) {
          $compileProvider.debugInfoEnabled(false)
        }

        $compileProvider.preAssignBindingsEnabled(true)

        $compileProvider.imgSrcSanitizationWhitelist(
          /^\s*((https?|ftp|file|blob|capacitor):|data:image\/)/
        )

        $compileProvider.aHrefSanitizationWhitelist(/^\s*(https?|s?ftp|mailto|tel|file|capacitor):/)

        $ariaProvider.config({
          tabindex: false,
          bindRoleForClick: false,
        })

        $ngReduxProvider.provideStore(store)
      }
    )
    .run(
      (
        $injector,
        $location,
        $q,
        $rootScope,
        $state,
        $stateParams,
        $window,
        anonymousIdService,
        auth,
        catalogCategoriesService,
        config,
        contextSelectorButtonService,
        eComLightService,
        list,
        maintenance,
        offersService,
        offline,
        oneCartService,
        reactRouterHistory,
        reservationService,
        unataCommService,
        userOverridesService,
        userOverridesV2Service,
        utilService
      ) => {
        // Sync React Router history with AngularJS Router
        reactRouterHistory.listen()
        eventEmitter.removeAllListeners(EVENT_TYPES.RELOAD_ANGULAR_STATE)

        // Fetch all data required to reset the state of the app
        // if a user changes store, etc..
        $rootScope.reloadState = function () {
          const deferred = $q.defer()
          $rootScope.isAppLoading = true
          $rootScope.$broadcast('reloadState')
          if ($state.current.name) {
            $state
              .transitionTo($state.current, $stateParams, {
                reload: true,
                inherit: false,
                notify: true,
                location: false,
              })
              .then(() => {
                deferred.resolve()
              })
              .catch(() => {
                // if unable to load content after store was changed, then navigate to home page
                if ($rootScope.deliveryStoreChangeIsInProgress) {
                  $state.go('main.home').then(() => {
                    deferred.resolve()
                  })
                } else {
                  deferred.reject()
                }
              })
              .finally(() => {
                // removing indication of recent store selection event
                $rootScope.deliveryStoreChangeIsInProgress = false
              })
          } else {
            $window.location.reload()
          }
          return deferred.promise
        }

        // Allow react app to reload angular state
        eventEmitter.on(EVENT_TYPES.RELOAD_ANGULAR_STATE, () => {
          $rootScope.reloadState()
        })

        // A promise to keep track of cases where the title is set asynchronously
        $rootScope.titlePromise = $q.defer()

        // Make all state changes wait for the presence of a user object
        $rootScope.$on('$stateChangeStart', (event, toState, toParams) => {
          const hasOverrideParamsInURL =
            Object.keys(toParams).filter(key => {
              return (
                toParams[key] !== null &&
                toParams[key] !== undefined &&
                userOverridesService.listOfOverrideParams.includes(key)
              )
            }).length > 0

          const goToStateCallback = (goOptions = { location: false }) => {
            utilService.removeSearchParametersGoToState(
              toState,
              toParams,
              [...userOverridesService.listOfOverrideParams, ...userOverridesV2Service.PARAM_NAMES],
              goOptions
            )
          }

          userOverridesV2Service.preCaptureContextParamOverrides(toParams)
          if (auth.onlyCateringEnabled()) {
            eComLightService.setMode(true)
          } else {
            eComLightService.enableEComLight()
          }

          if (!auth.user && !maintenance.active && !offline.isOffline) {
            // Halt state change
            event.preventDefault()

            auth
              .initializeUser(userOverridesService.getUserOverridePayload) // Pass this function in to avoid circular dependency issues
              .then(initializedUser => {
                // Initialize category tree
                catalogCategoriesService.fetchCategories(true)

                const isUserTrial = auth.user.status === 'trial'

                if (isUserTrial && config.gigya && config.gigya.enabled && config.gigya.syncLogin) {
                  const gigyaService = $injector.get('gigyaService')

                  return gigyaService.syncLogin().finally(goToStateCallback)
                }

                // This will not work if onecart is enabled via flagr
                if (oneCartService.enabled()) {
                  preloadCheckoutPage()
                }

                // Fetch user's reservation
                reservationService.fetch().catch(() => {})

                // If user is logged in...
                if (!isUserTrial) {
                  // Fetch user's save for later lists
                  store.dispatch(fetchUserLists())
                }

                anonymousIdService.init(initializedUser)

                // Here we fetch configs again so that any new
                // configs based on the current user can be loaded in.
                // Controllers reading from the config _constant_ will not
                // receive this update.
                return store.dispatch(fetchConfig())
              })
              .then(() => {
                if (
                  auth.user.status === 'trial' &&
                  config.auth?.serviceName &&
                  config.auth.serviceParams?.redirectTrialUser &&
                  toState.name !== 'socialSignOn'
                ) {
                  // Gated Unata experience redirect user to third party SSO before
                  // showing store content (currently only used by Costco)
                  const currentState = { state: toState, stateParams: toParams }
                  return auth.getAuthService().init(AUTH_ACTIONS.LOGIN, currentState)
                }
                // Parse user override parameters to see if we need to place the user in a certain store/context/zip on initialization
                if (hasOverrideParamsInURL) {
                  userOverridesService.parseOverrideParams(toParams)
                  userOverridesService
                    .updateExistingUserWithOverrides()
                    .then(() => goToStateCallback())
                } else {
                  goToStateCallback()
                }
              })
              .then(() => userOverridesV2Service.initializeContextParamOverrideSystem())
              .then(() => auth.initializeContext())
              .then(() => {
                if (!process.env.UNATA_WIDGETS) {
                  return
                }

                const event = new CustomEvent('unata-widgets-is-user-initialized')
                window.dispatchEvent(event)
              })
          } else if (hasOverrideParamsInURL) {
            // To support internal deep-linking.
            event.preventDefault()
            userOverridesService.parseOverrideParams(toParams)
            userOverridesService.updateExistingUserWithOverrides()
            goToStateCallback()

            // hack for sprouts header deeplinks && to be removed once react header goes in
            unataCommService.listen(unataCommService.EventType.CONFIRMCONTEXT, () => {
              auth.user.context.shopping_mode !== SHOPPING_MODES.CATERING &&
                userOverridesService.handleContextModalDeepLinks()
            })
          } else if (toParams['session-token']) {
            // If URL contains session-token param, after we process it (in auth service), remove it from the URL
            event.preventDefault()
            $state.go(toState, _.omit(toParams, ['session-token']))
            // TODO: Find a way to remove this callback once we've removed the param, so we don't have to keep checking on $stateChangeStart
          }
        })

        $rootScope.$on('$stateChangeSuccess', () => {
          userOverridesService.handleContextModalDeepLinks()
        })
      }
    )
    .config(($stateProvider, $uibTooltipProvider, $urlRouterProvider) => {
      // Configure the main state for the app which pre-resolves anything we need globally

      $stateProvider.state('main', {
        url:
          '?b2b_store&delivery_zip&hide_cart_compare_on_arrival&load-order&session-token&shopping_context&store&store_id&context&context_trial&context_authed&open_sidecart&confirm_context&enter_location&show_app_extended_info',
        abstract: true,
        resolve: {
          loadOrder(
            $q,
            $state,
            $stateParams,
            $timeout,
            cartService,
            config,
            errorModalService,
            list,
            oneCartService
          ) {
            // Load order to cart if specified in URL

            const orderId = $stateParams['load-order']
            if (orderId) {
              const modal = errorModalService.open(
                'Please wait while we load your order...',
                null,
                null,
                'Loading Order',
                true
              )
              return $q
                .all([
                  order.api.orders.get(orderId, {
                    one_cart_enabled: oneCartService.enabled(),
                    user_timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
                  }),
                  cartService.fetch(),
                ])
                .then(([order]) => list.loadListToCart(order.order_items))
                .then(() => {
                  modal.close()
                  return true
                })
            }
            return false
          },
        },
        onEnter(
          $location,
          $state,
          $timeout,
          $window,
          auth,
          config,
          express,
          loadOrder,
          loyaltyOnboardingService,
          // Make sure the mobileNativeHrefService service is loaded
          // to ensure windowManager.listen is called.
          mobileNativeHrefService,
          mobileOnboardingService,
          shoppingSelectorService,
          userOverridesService
        ) {
          // Runs all code needed to initialize the express service
          express.init()

          // Setting a flag to let third parties know that the platform app has loaded.
          if (!$window.unataLoaded) {
            $window.unataLoaded = true
          }

          if (loadOrder) {
            // If load-order param is used, once we've loaded the order we need to redirect to the checkout page
            $timeout(() => {
              $state.go('main.checkout', { 'load-order': null }, { location: 'replace' })
            })
          } else if (userOverridesService.openToStoreSelectorStep) {
            userOverridesService.openShoppingSelectorAfterOverride()
          }
          // If enter_location or confirm_context deeplinks are present, the below flow should be ignored
          else if (!userOverridesService.enterLocation && !userOverridesService.confirmContext) {
            const skipModals = config.skipModalPaths.indexOf($location.$$path) > -1

            // Below there are three different flows that can be triggered:
            // 1: Mobile Onboarding (mobileOnboardingService) this is a short carousel which happens if on mobile and if mobileOnboardingService.shouldShowOnboarding() is true
            // 2: Loyalty Onboarding (loyaltyOnboardingService). This is a series of three loyalty modal screens and happens if config.enableLoyaltyOnboarding and if the user is logged out
            // This happens after Mobile Onboarding on Mobile and before shoppingSelectorService (#3)
            // 3: Shopping Selector (shoppingSelectorService) This is the beginning of the unified flow. It is kicked off as the last step here.
            // Checks if the user is on mobile and has previously seen app onboarding
            mobileOnboardingService.shouldShowOnboarding().then(showOnboarding => {
              if (process.env.UNATA_WIDGETS) {
                return
              }

              if (!skipModals && showOnboarding) {
                mobileOnboardingService.openModal().then(() => {
                  // If user is logged out and loyalty onboarding is enabled show loyalty onboarding before initiating store selector
                  if (
                    config.enableLoyaltyOnboarding &&
                    auth.user.status === 'trial' &&
                    !loyaltyOnboardingService.hasSeenLoyalty &&
                    !skipModals
                  ) {
                    loyaltyOnboardingService
                      .initOnboarding('intro', null, null, null, LOGIN_CONTEXTS.LOYALTY_ONBOARDING)
                      .finally(() => {
                        shoppingSelectorService.homeStateContextHandler()
                      })
                  } else {
                    shoppingSelectorService.homeStateContextHandler(skipModals)
                  }
                })
              } else if (
                config.enableLoyaltyOnboarding &&
                auth.user.status === 'trial' &&
                !loyaltyOnboardingService.hasSeenLoyalty &&
                !skipModals
              ) {
                loyaltyOnboardingService
                  .initOnboarding('intro', null, null, null, LOGIN_CONTEXTS.LOYALTY_ONBOARDING)
                  .finally(() => {
                    shoppingSelectorService.homeStateContextHandler()
                  })
              } else {
                shoppingSelectorService.homeStateContextHandler(skipModals)
              }
            })
          }
        },
        views: {
          nav: {
            template: require('./components/nav/views/nav.html'),
            controller: 'NavController',
          },
          footer: {
            template: require('./components/footer/footer.html'),
            controller: 'FooterController',
          },
          sideCart: {
            template: `
              <div
                sticky
                side-cart
                side-cart-auto-focus="side-cart-close-button"
                offset="{{::stickyOffset}}"
                min-width="1250"
                sticky-check-callback="sideCartPositionChange"
                ng-if="config.oneCart.enabled || (!config.disableSideCart && !mobile.isMobile)"
                class="slideInRight"
                ng-class="{
                  'show-full-height': shouldShowSidecart() && isOneCartEnabled(),
                  show: shouldShowSidecart() && !isOneCartEnabled(),
                  hover: $root.isSideCartOpen,
                  'ecom-light-mode': inEComLightMode,
                  'non-sticky-height': isOneCartEnabled(),
                  'is-onecart-sidecart': isOneCartEnabled()
                }"
                aria-expanded="{{sideCartDropdownOpen}}"
                aria-controls="side-cart-container"
                data-test="side-cart-sticky-container"
              ></div>
            `,
          },
        },
      })

      // Fall back to 404 page when angular route not found
      $urlRouterProvider.otherwise($injector => {
        const $state = $injector.get('$state')
        $state.go('main.404', {}, { location: false })
      })

      // Set UI Bootstrap tooltip defaults
      $uibTooltipProvider.options({
        'popover-animation': false,
      })
    })
    .run(
      (
        $anchorScroll,
        $document,
        $rootScope,
        $templateCache,
        $timeout,
        $uibModalStack,
        analytics,
        config,
        lang,
        mobileService,
        qualitricsService,
        routingService,
        utilService,
        values
      ) => {
        $rootScope.config = config // Expose config in all scopes
        $rootScope.values = values
        $rootScope.analytics = analytics // Expose analytics in all scopes
        $rootScope.util = utilService
        $rootScope.mobile = mobileService
        $rootScope.isAppLoading = true // Trigger app loading indicator when the app starts
        $rootScope.lang = lang
        $rootScope.isReactDemo = isReactDemo

        // Trigger Qualitrics script when config enabled.
        if (config.qualitricsScript?.enabled) {
          qualitricsService.initQualitrics()
        }

        $rootScope.$on('$stateChangeSuccess', () => {
          if ($uibModalStack.getTop()) {
            // close all modal dialogs when changing pages (back button)
            $uibModalStack.dismissAll()

            // FIXME Find a way to preventDefault only in the case that the back button is pressed.
            // prevent page change in this instance
            // event.preventDefault();
          } else if (mobileService.isMobile) {
            // display loading indicator in mobile
            $rootScope.isAppLoading = true
          }
        })

        // Scroll to top when state changes and stop the app level loading indicator

        $rootScope.$on('$stateChangeSuccess', (event, toState, toParams, fromState, fromParams) => {
          $anchorScroll()
          // TODO: Remove once [ENT-7481] is done
          // keep the spinner up if this is a proxy call to a 3rd party authorization service and
          // `norender` param was passed
          const hasThirdPartyAuth = config.auth && config.auth.serviceName
          const hasNoRenderParam = toParams && (toParams.norender || fromParams.norender)
          if (hasThirdPartyAuth && hasNoRenderParam) {
            const el = $document[0].querySelector('.load-app.view-loading-indicator')
            if (el) {
              // make backdrop full white, no transparency
              el.className += ' no-render'
            }
            return
          }
          $rootScope.isAppLoading = false
          utilService.scrollToTop()
        })

        // Save the name of the previous state.
        $rootScope.$on('$stateChangeSuccess', (event, toState, toParams, fromState, fromParams) => {
          $rootScope.previousState = fromState.name
          $rootScope.previousStateParams = fromParams
        })

        const watchScope = $rootScope.$new()
        watchScope.$on('$stateChangeSuccess', () => {
          $rootScope.$broadcast('firstStateChangeSuccess')
          $timeout(() => {
            watchScope.$destroy()
          })
        })

        // For a11y utility, inform document when mouse is being used
        document.body.addEventListener('mousedown', () => {
          $document[0].body.classList.add('using-mouse')
        })

        // For a11y utility, inform document when the last key press was tab (likely using)
        document.body.addEventListener('keydown', event => {
          if (event.keyCode === 9) {
            $document[0].body.classList.remove('using-mouse')
          }
        })

        // Enable the 'mouse' mode, hiding the focus outline, by default.
        $document[0].body.classList.add('using-mouse')

        // This is a fix for iOS weird scrolling issues in inputs in fixed position elements.
        $rootScope.needsNativeIosStyleFix =
          mobileService.isMobileApp() && mobileService.getMobileOs() === DEVICE_TYPES.IOS

        // Fix accessibility with angular-carousel indicators
        $templateCache.put(
          'carousel-indicators.html',
          `<ul class="rn-carousel-indicator">
            <li ng-repeat="slide in slides">
              <button
                type="button"
                ng-class="{active: $index==index}"
                ng-click="goToSlide($index)"
                aria-label="{{::$parent.$root.lang.template($parent.$root.lang.aria.goToSlide, {index: $index + 1})}}"
                aria-current="{{$index===index}}"
              ></button>
            </li>
          </ul>`
        )

        // Fix accessibility with uib-popover
        $templateCache.put(
          'uib/template/popover/popover.html',
          `<div class="popover"
            tooltip-animation-class="fade"
            uib-tooltip-classes
            ng-class="{ in: isOpen() }"
            role="tooltip">
            <div class="arrow" aria-hidden="true"></div>

            <div class="popover-inner">
              <h3 class="popover-title" ng-bind="title" ng-if="title"></h3>
              <div class="popover-content" ng-bind="content"></div>
            </div>
          </div>`
        )

        // Fix accessibility with uib-popover-html
        $templateCache.put(
          'uib/template/popover/popover-html.html',
          `<div class="popover"
            tooltip-animation-class="fade"
            uib-tooltip-classes
            ng-class="{ in: isOpen() }"
            role="tooltip">
            <div class="arrow" aria-hidden="true"></div>

            <div class="popover-inner">
              <h3 class="popover-title" ng-bind="title" ng-if="title"></h3>
              <div class="popover-content" ng-bind-html="contentExp()"></div>
            </div>
          </div>`
        )

        // Fix accessibility with uib-popover-template
        $templateCache.put(
          'uib/template/popover/popover-template.html',
          `<div class="popover"
            tooltip-animation-class="fade"
            uib-tooltip-classes
            ng-class="{ in: isOpen() }"
            role="tooltip">
            <div class="arrow" aria-hidden="true"></div>

            <div class="popover-inner">
              <h3 class="popover-title" ng-bind="title" ng-if="title"></h3>
              <div class="popover-content"
                uib-tooltip-template-transclude="contentExp()"
                tooltip-template-transclude-scope="originScope()"></div>
            </div>
          </div>`
        )
      }
    )
    .run(($location, $rootScope, $state, $window, config, eComLightService) => {
      const performInternalRedirect = (destination, urlOverwrite, stateGoOptions) => {
        $state.go(destination.sref, destination.params, stateGoOptions).then(() => {
          if (urlOverwrite) {
            $location.url(urlOverwrite) // Replace the url so redirect is hidden from user
            // NOTE: `urlOverwrite` must match `toState` (state the user would've gone to had it not been redirected) url
            //        or else the user will end up just getting redirected to the urlOverwrite
          }
        })
      }

      // Allow state navigation to be overriden to an external URL or an internal state
      //  (external urls take precedence)

      if (
        config.eComLight.enabled ||
        (config.externalStates && !_.isEmpty(config.externalStates)) ||
        (config.internalRedirects && !_.isEmpty(config.internalRedirects))
      ) {
        $rootScope.$on('$stateChangeStart', (event, toState, toParams, fromState) => {
          const fromStateName = fromState && fromState.name
          const toStateName = toState && toState.name

          if (config.externalStates[toStateName]) {
            // Handle external redirects

            event.preventDefault()
            $window.open(config.externalStates[toStateName], 'external')
            $rootScope.isAppLoading = false
          } else {
            // Handle internal redirects
            // eslint-disable-next-line no-lonely-if
            if (
              eComLightService.inEComLightMode &&
              eComLightService.isForbiddenState(toStateName, toParams)
            ) {
              // If a user tries to access a route/page we cannot show them in eCom Light mode,
              //  we need to redirect them to config.eComLight.homeRedirect
              event.preventDefault()
              performInternalRedirect(config.eComLight.homeRedirect, '/', { reload: true })
            } else if (config.internalRedirects[toStateName] && fromStateName !== toStateName) {
              const destination = config.internalRedirects[toStateName]

              event.preventDefault()
              performInternalRedirect(destination)
            }
          }
        })
      }
    })
    .run(($document, $rootScope, config, isBarebone) => {
      // Pull in the configured CMS provided scripts if Header Footer Sync is enabled.
      if (
        config.dotComModule.enableHeaderFooterSync &&
        !config.react?.enableHeader &&
        !$rootScope.isReactDemo() &&
        !isBarebone
      ) {
        const head = $document[0].head || $document[0].getElementsByTagName('head')
        config.dotComModule.headerSyncUrl.scripts.forEach(url => {
          const scriptTag = document.createElement('script')
          scriptTag.src = `${url}?t=${config.timestamp}`
          head.appendChild(scriptTag)
        })
      }
    })
    .run((unataCommInterceptorService, unataCommService) => {
      // Attach UnataCommService listeners
      unataCommService.listen(unataCommService.EventType.GETADDITIONALCONTEXTDETAILS, () => {
        unataCommInterceptorService.sendAdditionalContextDetails()
      })

      unataCommService.listen(unataCommService.EventType.UNATADEBUGMODE, event => {
        unataCommInterceptorService.handleDebugEvents(event)
      })
    })
    .run($rootScope => {
      if (!process.env.UNATA_WIDGETS) {
        return
      }

      $rootScope.$watch(
        'showingSideCart',
        debounce(showingSideCart => {
          document.body.classList.toggle('unata-side-cart', showingSideCart)
        }, 1500)
      )
    })
}
