/**
 * AngularJS App Renderer
 *
 * On mount, this renders the AngularJS app.
 *
 * When the parent React Route is NOT on an active React route, the AngularJS
 * app is displayed in the DOM. When on an active React route, the AngularJS
 * app is hidden. This is a workaround to prevent bootstrapping the AngularJS
 * every time the user navigates from a React to an AngularJS route.
 */

import React, { useEffect, useRef } from 'react'
import angular from 'angular'
import AngularAppContext from './AngularAppContext'
import { useIsReactRoute } from './routes'
import { setupAngularApp } from '../angular/app'

const AngularApp = () => {
  const angularRootEl = useRef<HTMLDivElement>(null)
  const isReactRoute = useIsReactRoute()
  const { setIsReady, angularJSDependencies, isBarebone } = React.useContext(AngularAppContext)

  useEffect(() => {
    if (angularRootEl.current) {
      setupAngularApp(isBarebone)
      angular.bootstrap(angularRootEl.current, ['unata', ...(angularJSDependencies ?? [])])
      setIsReady(true)
    }
  }, [setIsReady, angularRootEl, angularJSDependencies, isBarebone])

  useEffect(() => {
    if (!process.env.UNATA_WIDGETS) {
      return
    }

    const event = new CustomEvent('unata-widgets-is-app-loaded')
    window.dispatchEvent(event)
  }, [])

  return (
    <div
      id="angular"
      ref={angularRootEl}
      className="angular-root"
      ng-class="{
        'show-side-cart': $root.showingSideCart,
        'is-modify-cart': $root.modifyingOrder,
        'side-cart-open': $root.isSideCartOpen,
        'wk': $root.mobile.iosWebViewCheck()
      }"
      style={{ display: isReactRoute ? 'none' : 'block' }}
      aria-hidden={isReactRoute ? 'true' : 'false'}
    >
      {!isBarebone && (
        <>
          <div
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore non-standard html attribute
            overlay="true"
            className="storefront-overlay"
            ng-class="{ 'active': $root.isSideCartOpen && config.oneCart.enabled && !mobile.isMobile }"
            react-storefront-builder="react"
          />
          <div ui-view="nav" />
          <div ui-view="sideCart" react-storefront-builder="react-sidecart" />
          <div
            ng-if="$root.isAppLoading"
            data-test="global-app-loading"
            className="load-app view-loading-indicator"
          />
          <main
            ui-view="content"
            ng-class="{
          'banner-fix': $root.headerBanners.length > 0,
          'show-overflow': $root.showOverflow,
          'one-cart': config.oneCart.enabled,
          'react-header-enabled': config.react.enableHeader,
          'sidecart-padding': !(config.oneCart.enabled || config.react.enableHeader) && !mobile.isMobile && $root.showingSideCart
        }"
            id="content"
          />
          <div ui-view="fixed-content" className="fixed-content" />
          <div ui-view="footer" ng-if="!mobile.isMobile" />
        </>
      )}
    </div>
  )
}

export default AngularApp
