import angular from 'angular'
import template from './views/recaptchaCheckbox.html'

export default angular.module('recaptchaModule', []).component('recaptchaCheckbox', {
  bindings: {
    action: '<',
    onExpired: '<?',
    onError: '<?',
    onRender: '<?',
    onSuccess: '<?',
  },
  controller($document, $interval, $element, config, recaptchaService) {
    const vm = this
    vm.renderIncrement = 0
    vm.renderInterval = null

    window.onRecaptchaCheckboxSuccess = vm.onSuccess
    window.onRecaptchaCheckboxExpired = vm.onExpired
    window.onRecaptchaCheckboxError = vm.onError

    vm.siteKey = config.googleRecaptcha?.siteKey

    if (!vm.siteKey) {
      console.error('Missing Google reCAPTCHA Site Key!')
    }

    vm.renderCheckbox = () => {
      // eslint-disable-next-line no-plusplus
      vm.renderIncrement++
      const root = $element.children()[0]
      // We must remove old nodes/reCAPTCHA mount points
      // A different DOM element is required for mounting each time
      root.innerHTML = ''

      // Create checkbox
      const checkbox = document.createElement('div')
      const checkboxId = `g-recaptcha-${vm.action}-${vm.renderIncrement}`
      checkbox.id = checkboxId
      checkbox.dataset.sitekey = vm.siteKey
      checkbox.dataset.action = vm.action
      checkbox.dataset.callback = 'onRecaptchaCheckboxSuccess'
      checkbox.dataset.expiredCallback = 'onRecaptchaCheckboxExpired'
      checkbox.dataset.errorCallback = 'onRecaptchaCheckboxError'

      let promise
      if ($document[0] instanceof ShadowRoot) {
        const slot = document.createElement('slot')
        slot.name = 'recaptcha'
        promise = new Promise(resolve => {
          slot.addEventListener('slotchange', () => {
            const [root] = slot.assignedElements()
            if (!root) {
              return
            }

            root.replaceChildren(checkbox)
            const result = recaptchaService.renderCheckbox(checkbox)
            resolve(result)
          })
        })
        root.querySelector('slot')?.remove()
        root.appendChild(slot)
      } else {
        root.appendChild(checkbox)
        promise = recaptchaService.renderCheckbox(checkbox)
      }

      return promise
    }

    vm.$onInit = () => {
      vm.renderCheckbox().then(() => vm.onRender && vm.onRender())

      // Unfortunately, the client signature expires every ~2 min.
      // Once this happens, we MUST destory the old checkbox, get
      // a new client signature, and re-create the checkbox with it
      vm.renderInterval = $interval(() => vm.renderCheckbox(), 110000)
    }

    vm.$onDestroy = () => {
      delete window.onRecaptchaCheckboxSuccess
      delete window.onRecaptchaCheckboxExpired
      delete window.onRecaptchaCheckboxError

      if (vm.renderInterval) {
        $interval.cancel(vm.renderInterval)
      }
    }
  },
  template,
})
