import _ from 'lodash'
import {
  VANTIV,
  VANTIV_CVV_SUCCESS_RESPONSES,
  VANTIV_AVS_SUCCESS_RESPONSES,
} from '@app/angular/constants/paymentGateway'
import angular from 'angular'
import { PAYMENT_POLYTYPES } from '@app/angular/constants/payments'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import template from './vantivReturn.html'

const COMPLETE = 'Complete'
const CANCELLED = 'Cancelled'
const ERROR = 'Error'
const VANTIV_LOST_CONNECTION = 'Connection between Vantiv lost'
const CONNECTION_TIMEOUT_STATUS = -1

export default angular
  .module('VantivIntegrationControllerModule', [])

  .config($stateProvider => {
    $stateProvider.state('vantiv', {
      url: VANTIV.URL,
      data: {},
      views: {
        'content@': {
          template,
          controller($window, $stateParams) {
            $window.parent.postMessage($stateParams, '*')
          },
        },
      },
    })
  })
  .controller(
    'VantivIntegrationController',
    function (
      $location,
      $q,
      $scope,
      $timeout,
      $window,
      Restangular,
      config,
      errorModalService,
      lang,
      recaptchaService,
      utilService,
      vantivPayment
    ) {
      const onShowCardForm = $scope.onShowCardForm || $scope.$parent.onShowCardForm
      const checkoutNewCreditCard =
        $scope.checkoutNewCreditCard || $scope.$parent.checkoutNewCreditCard
      const setCreditCard = () => $scope.doSave && $scope.doSave()

      let iframe = getIframe()
      let existingPaymentInstruments = []

      $scope.showAddInstrument = false

      if ($scope.toggleIsShowingAddCard) {
        $scope.toggleIsShowingAddCard(false)
      }

      $scope.showAddInstrumentLoader = false

      /**
       * Gets the transaction_setup_id for the payment
       *
       * @param {object} [recaptcha]   - Optional: If recaptcha is switched on, a recaptcha object to
       *                                            send on the PUT request to validate non-robot usage
       * @returns {Promise}
       */
      $scope.getAuthToken = function (recaptcha = {}) {
        let baseUrl = `${$location.$$protocol}://${$location.$$host}`
        if ($location.$$port) {
          baseUrl += `:${$location.$$port}`
        }
        return Restangular.one('cart')
          .one('payment')
          .customPUT({
            payment_type: 'auth_capture',
            auth_capture: { web_url: baseUrl },
            recaptcha,
          })
          .then(response => ($scope.auth_token = response.auth_capture.transaction_setup_id))
      }

      $scope.clearVantivIframe = function () {
        iframe.src = ''
      }

      $scope.loadVantivIframe = function (transactionSetupId) {
        iframe.src = VANTIV.IFRAME_FN({ isProd: utilService.isProd(), transactionSetupId })
      }

      $scope.toggleAddInstrumentLoader = function (isShow) {
        $scope.showAddInstrumentLoader = !!isShow
      }

      $scope.getPaymentInstruments = function (initCall, selectExtId) {
        return Restangular.one('user')
          .one('payment')
          .get({ payment_type: 'auth_capture' })
          .then(response => {
            vantivPayment.status.loaded = true

            if (initCall) {
              // set an existing object to compare with newly added payment instruments in this state load
              existingPaymentInstruments = JSON.parse(
                angular.toJson(response.auth_capture.payment_instruments)
              )
              $scope.paymentInstruments = response.auth_capture.payment_instruments
            } else {
              // apply an `isNew` property to any newly added payment instruments
              const sanitizedExistingPaymentInstruments = JSON.parse(
                angular.toJson(existingPaymentInstruments)
              )

              $scope.paymentInstruments = _.map(
                response.auth_capture.payment_instruments,
                paymentInstrument => {
                  paymentInstrument.isNew = !_.find(
                    sanitizedExistingPaymentInstruments,
                    existingPaymentInstrument => {
                      return existingPaymentInstrument.id === paymentInstrument.id
                    }
                  )

                  if (
                    paymentInstrument.isNew &&
                    typeof paymentInstrument.shouldSave !== 'boolean'
                  ) {
                    paymentInstrument.shouldSave = true
                  }

                  return paymentInstrument
                }
              )
            }

            if ($scope.paymentInstruments.length) {
              const instrument =
                selectExtId &&
                $scope.paymentInstruments.find(instrument => instrument.ext_id === selectExtId)

              if (instrument) {
                $scope.paymentInstrumentId = instrument.id
              } else if (!$scope.paymentInstrumentId) {
                $scope.paymentInstrumentId = $scope.paymentInstruments[0].id
              } else {
                const stillExists = !!_.find($scope.paymentInstruments, {
                  id: $scope.paymentInstrumentId,
                })
                if (!stillExists) {
                  $scope.paymentInstrumentId = $scope.paymentInstruments[0].id
                }
              }

              if (initCall) {
                $timeout(setCreditCard)
              }
            } else {
              $scope.paymentInstrumentId = null
            }

            // Save paymentInstruments and the state of their `isNew` and `shouldSave` properties for further processing at Order Submit
            vantivPayment.paymentInstruments = $scope.paymentInstruments

            // If no credit cards, open add new card immediately
            if (!$scope.paymentInstruments?.length) {
              $scope.addPaymentInstrument()
            }
          })
      }

      const loadInitial = () => $scope.getPaymentInstruments(true, $scope.payment?.auth_capture)

      if (iframe) {
        loadInitial()
      }

      $scope.addPaymentInstrument = function (isRetry) {
        $scope.clearVantivIframe()
        $scope.recaptcha = {}
        $scope.isRecaptchaReady = false
        $scope.showAddInstrument = true

        if ($scope.toggleIsShowingAddCard) {
          $scope.toggleIsShowingAddCard(true)
        }

        $scope.toggleAddInstrumentLoader(true)

        reloadPaymentInfo(isRetry)
          .then(() => {
            if (!getIframe()) throw CANCELLED
            // Upon render, $scope.onRecaptchaReady will get triggered
            return vantivPayment.initializeRecaptcha(
              // If $scope.reactMethods is defined, we want to show a popup for recaptcha
              !$scope.reactMethods ? $scope.recaptcha : recaptchaService.openRecaptchaInModal
            )
          })
          .then(recaptchaResponse => {
            $scope.recaptcha = { complete: true }
            $scope.isRecaptchaReady = false
            $scope.toggleAddInstrumentLoader(true)
            return $scope.getAuthToken(recaptchaResponse)
          })
          .then($scope.loadVantivIframe) // Passes in authToken as the first argument
          .then(() => {
            $scope.toggleAddInstrumentLoader(false)
            if (onShowCardForm) {
              onShowCardForm()
            }
          })
          .then(addVantivHandlerAndWait)
          .then(ext_id => $scope.getPaymentInstruments(false, ext_id))
          .then(() => $timeout(setCreditCard))
          .then(() => {
            if (checkoutNewCreditCard) {
              checkoutNewCreditCard(true)
            }
          })
          .catch(err => {
            addCardFailed(!err.isRecaptchaError && isRetry, err.isRecaptchaAbort ? CANCELLED : err)
          })
          .finally(() => {
            $scope.recaptcha = {}
            $scope.isRecaptchaReady = false
          })
      }

      $scope.onRecaptchaReady = () => {
        // It's a lie, it's not really ready for another ~1s!
        // This is to prevent flicker of the captcha
        $timeout(() => {
          $scope.toggleAddInstrumentLoader(false)
          $scope.isRecaptchaReady = true
        }, 1000)
      }

      function getIframe() {
        return document.querySelector('#iframeVantiv')
      }

      function reloadPaymentInfo(isRetry) {
        return Restangular.one('user')
          .one('payment')
          .get({ payment_type: 'auth_capture', UI_isRetry: isRetry })
      }

      function addVantivHandlerAndWait() {
        return new Promise((resolve, reject) => {
          if (!$window.addEventListener) {
            $window.attachEvent('onmessage', e => vantivReturnHandler(e, resolve, reject))
          } else {
            $window.addEventListener('message', e => vantivReturnHandler(e, resolve, reject), false)
          }
        })
      }

      $scope.backToCardView = () => {
        $scope.showAddInstrument = false
        $scope.isRecaptchaReady = false
        $scope.recaptcha = {}

        if ($scope.toggleIsShowingAddCard) {
          $scope.toggleIsShowingAddCard(false)
        }

        $scope.toggleAddInstrumentLoader(false)
        $scope.clearVantivIframe()
      }

      function addCardFailed(isRetry, err) {
        $scope.backToCardView()

        if (err === CANCELLED) return

        if (!isRetry) {
          $scope.addPaymentInstrument(true)
        } else {
          let errorMessage = err.data?.message || lang.values.defaultErrorMessage
          if (VANTIV_LOST_CONNECTION || err.status === CONNECTION_TIMEOUT_STATUS) {
            errorMessage = `${lang.values.timedout} ${lang.values.tryAgainPolite}.`
          } else if (err.status === HTTP_ERRORS.TOO_MANY_REQUESTS) {
            errorMessage = lang.values.tooManyRetriesError
          }

          errorModalService.open(errorMessage, lang.values.ok)

          if (checkoutNewCreditCard) {
            checkoutNewCreditCard(false)
          }
        }
      }
      // The vantiv return handler gets called multiple times sometimes,
      // This is  needed to keep a record of transaction so we don't have more than one modal
      $scope.prevTransactionId = null
      function vantivReturnHandler(e, resolve, reject) {
        const status = e.data.HostedPaymentStatus
        switch (status) {
          case CANCELLED:
            return reject(CANCELLED)
          case ERROR:
            return reject(e)

          // vantivReturnHandler is triggered multiple times, most of the times are nonsense, so we ignore by returning
          default:
            if (status !== COMPLETE) return
        }

        const { CVVResponseCode, AVSResponseCode } = e.data || {}
        const isCvvResponseOk =
          CVVResponseCode && VANTIV_CVV_SUCCESS_RESPONSES.includes(CVVResponseCode)
        const isAvsResponseOk =
          AVSResponseCode && VANTIV_AVS_SUCCESS_RESPONSES.includes(AVSResponseCode)

        if (!isCvvResponseOk || !isAvsResponseOk) {
          // This is needed to ensure we don't display multiple modals as this function can be called multiple times
          if (e.data.TransactionID === $scope.prevTransactionId) {
            return reject(e)
          }

          $scope.prevTransactionId = e.data.TransactionID
          errorModalService.open(
            lang.values[!isCvvResponseOk ? 'cvvError' : 'avsError'],
            lang.values.ok
          )
          return reject(e)
        }

        $scope.showAddInstrument = false

        if ($scope.toggleIsShowingAddCard) {
          $scope.toggleIsShowingAddCard(false)
        }

        $scope.clearVantivIframe()

        const paymentInstrument = {
          polytype: PAYMENT_POLYTYPES.CREDIT_CARD,
          last_four_digits: e.data.LastFour,
          ext_id: e.data.PaymentAccountID,
          expiry_date: '3000-01-01',
          provider: e.data.CardLogo,
          holder_name: null,
          name: 'card',
          ext_data: { transaction_id: e.data.TransactionID },
        }

        $window.removeEventListener('message', vantivReturnHandler, false)

        return Restangular.one('user')
          .one('payment')
          .customPOST(paymentInstrument)
          .then(response => ($scope.auth_token = response.ext_id))
          .then(resolve)
          .catch(reject)
      }

      $scope.deleteCard = function (card) {
        errorModalService
          .open(
            `${lang.template(lang.values.deleteCardMessage, {
              cardName: card.name,
              cardNumber: card.last_four_digits,
            })}`,
            lang.values.confirm,
            lang.values.cancel,
            lang.values.deleteCard
          )
          .then(() => Restangular.one('user/payment', card.id).remove())
          .then(() => $scope.getPaymentInstruments())
      }

      $scope.$watch('paymentInstrumentId', () => {
        if ($scope.payment) {
          $scope.payment.auth_capture = {
            payment_instrument: {
              id: $scope.paymentInstrumentId,
            },
          }
        }
        vantivPayment.status.ready = !!$scope.paymentInstrumentId
      })

      $scope.$watch(
        () => $scope.lastEditMode,
        isLastEditMode =>
          isLastEditMode && $scope.showAddInstrument && $scope.toggleIsShowingAddCard(true)
      )

      const findCard = extId => $scope.paymentInstruments?.find(card => card.ext_id === extId)

      /**
       * React JS supporting methods for payments
       *
       * If there is a parent controller present `$scope.reactMethods` can be defined, to allow
       * these methods to be accessed by the parent scope. This is because objects are passed by
       * reference, not by value.
       */
      if ($scope.reactMethods) {
        Object.assign($scope.reactMethods, {
          onDeleteCard: extId => $scope.deleteCard(findCard(extId)),
          onUpdateCard: (extId, { saveInProfile }) => {
            $scope.$apply(() => {
              $scope.paymentInstruments = $scope.paymentInstruments.map(card => {
                const shouldSave = card.ext_id === extId ? saveInProfile : card.saveInProfile
                return { ...card, shouldSave }
              })
              vantivPayment.paymentInstruments = $scope.paymentInstruments
            })
          },
          onPaymentIframeRendered: renderedIframe => {
            iframe = renderedIframe
            loadInitial()
          },
          onPurchaseComplete: () => vantivPayment.checkoutComplete(),
          onSelectCard: extId => $scope.$apply(() => ($scope.paymentInstrumentId = extId)),
          getSelectedCard: () => {
            const { paymentInstrumentId: idOrExtId, paymentInstruments: cards } = $scope
            if (cards) {
              const found = cards.find(({ id, ext_id }) => id === idOrExtId || ext_id === idOrExtId)
              return found?.ext_id
            }
          },
        })
      }
    }
  )
