import _ from 'lodash'
import angular from 'angular'
import { CART_TYPES } from '@app/angular/constants/cart'
import getApiPrefix from '@app/utils/getApiPrefix'
import { utils } from '@instacart/enterprise-services'

const apiPrefix = getApiPrefix()

export default angular
  .module('restangularServicesModule', [])
  .config(RestangularProvider => {
    RestangularProvider.setBaseUrl(apiPrefix)

    RestangularProvider.addResponseInterceptor((data, operation) => {
      if (operation === 'getList') {
        const meta = _.omit(data, 'items')
        data = data.items || []
        _.each(meta, (item, key) => {
          data[key] = item
        })

        angular.forEach(data, item => {
          if (item.href) {
            item.href = apiPrefix + item.href
          }
        })
      }

      if (data && data.href) {
        data.href = apiPrefix + data.href
      }

      return data
    })
  })
  .run(($q, Restangular, auth, configConstants, eComLightService) => {
    Restangular.addFullRequestInterceptor(
      (element, operation, _what, url, headers, params, httpConfig) => {
        const debugMode = localStorage?.getItem('DEBUG')?.includes('logRestangular=1')
        // useful to find what code initiated a request
        if (debugMode) {
          console.trace(url)
        }
        if (auth.user && auth.user.store && auth.user.store.id) {
          const headerObj = {
            StoreId: auth.user.store.id,
            FulfillmentType: auth.getContext().intent,
          }
          if (auth.getContext() && auth.getContext().deliveryPostalCode) {
            _.assign(headerObj, { DeliveryPostalCode: auth.getContext().deliveryPostalCode })
          }
          const headerObjJsonStrB64 = btoa(JSON.stringify(headerObj))
          headers['User-Context'] = headerObjJsonStrB64
        }
        if (configConstants.includeRequestSource) {
          headers['X-Unata-Request-Source'] = 'platform'
        }

        // Send cookies for api requests when session token usage is disabled
        if (!utils.sessionToken.shouldUseToken() && url.indexOf(apiPrefix) !== -1) {
          angular.extend(httpConfig, { withCredentials: true })
        }

        // Include the `X-Unata-Mode` header in all but Wordpress API calls since
        // the Wordpress API does not CORS whitelist this header.
        if (!url.includes('wp-json')) {
          headers['X-Unata-Mode'] = eComLightService.inEComLightMode
            ? CART_TYPES.CATERING
            : CART_TYPES.GROCERY
        }

        // set content-type for compatibility with the backend
        headers['Content-Type'] = 'application/json'
      }
    )
  })
