import angular from 'angular'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'

export default angular
  .module('savedRecipesServiceModule', [])
  .service(
    'savedRecipesService',
    function savedRecipesService($state, Restangular, analytics, auth, registrationService) {
      const service = this

      service.savedRecipesList = []

      service.getSavedRecipes = () => {
        return Restangular.one('recipes')
          .one('saved')
          .get()
          .then(savedRecipes => {
            service.savedRecipesList = savedRecipes.items
            return savedRecipes.items
          })
          .catch(() => null)
      }

      service.requireLogin = () => {
        return new Promise((resolve, reject) => {
          if (auth.user.status === 'trial') {
            return registrationService
              .open('login', null, null, null, null, null, LOGIN_CONTEXTS.SAVE_RECIPE)
              .then(res => {
                if (res === 'failed') {
                  return reject()
                }
                // Refresh the current state
                $state.go($state.current.name, { reload: true })
                return resolve()
              })
              .catch(() => reject())
          }
          return resolve()
        })
      }

      service.saveRecipe = (recipeId, recipe, index) =>
        Restangular.one('recipes')
          .one('saved')
          .post(recipeId)
          .then(() => {
            service.savedRecipesList = [...service.savedRecipesList, { id: recipeId }]

            analytics.logSegmentEvent('recipe.save_recipe', {
              is_saved: true,
              recipe_details: analytics.getRecipeAnalytics(recipe, index),
            })
          })

      service.removeSavedRecipe = (recipeId, recipe, index) =>
        Restangular.one('recipes')
          .one('saved', recipeId)
          .remove()
          .then(() => {
            let newSavedRecipeList = [...service.savedRecipesList]
            newSavedRecipeList = newSavedRecipeList.filter(recipe => recipe.id !== recipeId)
            service.savedRecipesList = newSavedRecipeList

            analytics.logSegmentEvent('recipe.save_recipe', {
              is_saved: false,
              recipe_details: analytics.getRecipeAnalytics(recipe, index),
            })
          })
    }
  )
