import angular from 'angular'
import template from '../templates/cartListTransferModal/cartListTransferModal.html'

export default angular
  .module('cartListTransferModalModule', [])
  .service('cartListTransferModalService', function cartListTransferModalService($uibModal) {
    this.open = ({ initialContext, updatedContext, itemCount }) =>
      $uibModal.open({
        template,
        controller(lang, initialContext, updatedContext, itemCount) {
          const $ctrl = this

          $ctrl.lang = lang
          $ctrl.itemCount = itemCount
          $ctrl.initialContext = initialContext
          $ctrl.updatedContext = updatedContext
        },
        resolve: {
          initialContext: () => initialContext,
          updatedContext: () => updatedContext,
          itemCount: () => itemCount,
        },
        controllerAs: '$ctrl',
      }).result
  })
