import angular from 'angular'

export default angular
  .module('SideCartDirectiveAutoFocusModule', [])
  .directive('sideCartAutoFocus', function ($rootScope, utilService) {
    return {
      restrict: 'A',
      link: (scope, element, attrs) => {
        scope.$watch(
          () => $rootScope.isSideCartOpen,
          (isSideCartOpen, wasSideCartOpen) => {
            if (isSideCartOpen && !wasSideCartOpen) {
              utilService.trapFocusOn(element[0])
              utilService.focusSelector(`.${attrs.sideCartAutoFocus}`)
            }
          }
        )
      },
    }
  })
