import angular from 'angular'

export default angular
  .module('loyaltyServicesModule', [])
  .service(
    'loyaltyService',
    function loyaltyService($state, auth, cartService, config, lang, userService) {
      const service = this

      service.showLoyaltyPlacement = () =>
        config.enableLoyaltyPricing &&
        (!userService.loyaltyCards.length || auth.user.status === 'trial')

      /**
       * This method accepts a rewards *source* object. It iterates over it and returns
       * only the visible ones
       *
       * @param {object} - source
       * @returns {object[]}
       */
      service.getVisibleRewards = (source = {}) => {
        const { rewards } = config

        const rewardsMap = rewards.visible.map(rewardKey => {
          const rewardLang = lang.values.rewards[rewardKey]
          const { altLabel, error, label, primaryInfo, secondaryInfo } = rewardLang || {}
          const rewardBase = {
            id: rewardKey,
            label: label || rewardKey,
            altLabel: altLabel || rewardKey,
          }
          const sourceReward = source[rewardKey]
          let amount
          let visible

          // if the reward value is an object
          if (sourceReward && typeof sourceReward === 'object') {
            amount = sourceReward.amount
            visible = sourceReward.visible
          }

          // if the reward value is a string
          if (typeof sourceReward === 'string') {
            amount = sourceReward
            visible = true
          }

          const rewardAmount = -Math.abs(parseFloat(amount))

          return Object.keys(source).includes(rewardKey)
            ? {
                ...rewardBase,
                amount: rewardAmount,
                primaryInfo,
                visible,
                secondaryInfo: lang.template(secondaryInfo, {
                  link: $state.href(rewards?.details?.[rewardKey]?.state, {}),
                }),
              }
            : { ...rewardBase, amount: 0, primaryInfo: error, secondaryInfo: '', visible: true } // show error
        })

        return rewardsMap.filter(reward => reward.visible)
      }
    }
  )
