import angular from 'angular'

export default angular
  .module('NativeMobileBrightnessServiceServicesModule', [])
  .service('nativeMobileBrightnessService', function nativeMobileBrightnessService(mobileService) {
    const service = this

    const maxBrightness = 1
    let brightnessPlugin
    let savedBrightness = null
    let brightnessIsPaused = false

    service.pauseFullBrightnessMode = () => {
      if (savedBrightness !== null) {
        service.setBrightness(savedBrightness)
        brightnessIsPaused = true
      }
    }

    service.resumeFullBrightnessMode = () => {
      if (brightnessIsPaused) {
        service.setBrightnessToMax()
        brightnessIsPaused = false
      }
    }

    service.enableFullBrightnessMode = () => {
      service.backupCurrentBrightness(service.setBrightnessToMax)
    }

    service.resetBrightnessMode = () => {
      service.setBrightness(savedBrightness, () => {
        savedBrightness = null
        brightnessIsPaused = false
      })
    }

    service.backupCurrentBrightness = (callback, fail) => {
      brightnessPlugin &&
        brightnessPlugin.getBrightness(brightness => {
          if (savedBrightness === null) {
            savedBrightness = brightness
          }
          callback && callback()
        }, fail)
    }

    service.setBrightness = (brightness, callback, fail) => {
      brightnessPlugin &&
        brightness !== null &&
        brightnessPlugin.setBrightness(brightness, callback, fail)
    }

    service.setBrightnessToMax = () => {
      brightnessPlugin && brightnessPlugin.setBrightness(maxBrightness)
    }

    document.addEventListener('deviceready', () => {
      brightnessPlugin = mobileService.isMobileApp() && window.cordova.plugins.brightness
    })
    document.addEventListener('pause', service.pauseFullBrightnessMode, false)
    document.addEventListener('resume', service.resumeFullBrightnessMode, false)
  })
