import React from 'react'
import PropTypes from 'prop-types'
import { LoadingBlock, createStyles } from '@instacart/cocktail'

const loadingUseStyles = createStyles(tokens => ({
  margins: {
    marginBottom: tokens.size.layout.xxSmall,
    marginTop: tokens.size.layout.xxSmall,
  },
  nowrap: {
    overflow: 'hidden',
    whiteSpace: 'nowrap',
  },
  inline: {
    display: 'inline-block',
  },
  root: {
    minHeight: 68,
  },
}))

const TimeSlotsLoading = ({ styles, alwaysShowAvailabilityCheckbox, ...restProps }) => {
  const loadingStyles = loadingUseStyles(restProps)

  return (
    <div css={[styles.root, loadingStyles.root]} data-test="time-slots-loading">
      <div
        css={[
          styles.titleRow,
          loadingStyles.margins,
          !alwaysShowAvailabilityCheckbox ? styles.titleRowWithoutCheckbox : {},
        ]}
      >
        {alwaysShowAvailabilityCheckbox ? (
          <LoadingBlock shape="square" height={20} width={180} />
        ) : (
          <></>
        )}
        <LoadingBlock shape="square" height={32} width={180} css={styles.dayPager} />
      </div>
      <div css={[styles.days, loadingStyles.margins, loadingStyles.noWrap]}>
        {Array(7)
          .fill()
          .map((val, index) => (
            <LoadingBlock
              // eslint-disable-next-line react/no-array-index-key
              key={`day-loading-block-${index}`}
              shape="square"
              css={[styles.day, loadingStyles.inline]}
              height={64}
              width={64}
            />
          ))}
      </div>
      <div>
        {Array(6)
          .fill()
          .map((val, index) => (
            <LoadingBlock
              // eslint-disable-next-line react/no-array-index-key
              key={`time-slot-loading-block-${index}`}
              shape="square"
              height={42}
              width={'100%'}
              css={loadingStyles.margins}
            />
          ))}
      </div>
    </div>
  )
}

TimeSlotsLoading.propTypes = {
  /** The styles from the timeslots component */
  styles: PropTypes.shape({
    // eslint-disable-next-line react/forbid-prop-types
    root: PropTypes.object,
    // eslint-disable-next-line react/forbid-prop-types
    titleRow: PropTypes.object,
    // eslint-disable-next-line react/forbid-prop-types
    dayPager: PropTypes.object,
    // eslint-disable-next-line react/forbid-prop-types
    days: PropTypes.object,
    // eslint-disable-next-line react/forbid-prop-types
    day: PropTypes.object,
    // eslint-disable-next-line react/forbid-prop-types
    title: PropTypes.object,
    // eslint-disable-next-line react/forbid-prop-types
    titleRowWithoutCheckbox: PropTypes.object,
  }),
  /** Shows only available timeslots and takes away the unavailable functionality */
  alwaysShowAvailabilityCheckbox: PropTypes.bool,
}

export default TimeSlotsLoading
