import angular from 'angular'
import template from './vantivIntegration.html'

export default angular
  .module('VantivPaymentServiceModule', [])
  .config(paymentGatewayProvider => {
    paymentGatewayProvider.registerGateway({
      name: 'Vantiv',
      key: 'auth_capture',
      template,
      serviceName: 'vantivPayment',
      newCheckoutCssClass: 'provider-generic_styling',
    })
  })

  .service('vantivPayment', function vantivPayment($q, Restangular, defaultPayments) {
    const service = this

    service.status = {}

    service.checkout = defaultPayments.checkout
    service.checkoutComplete = () => {
      service.paymentInstruments.forEach(({ id, isNew, shouldSave }) => {
        // Payment instrument saved by default - set to false for those we're not saving and PUT this to server
        if (isNew === true && !shouldSave) {
          Restangular.one('user').one('payment', id).customPUT({ id, status: false })
        }
      })
    }

    service.initializeRecaptcha = defaultPayments.initializeRecaptcha

    service.paymentInstruments = []
  })
