import _ from 'lodash'
import angular from 'angular'
import template from './facetedSideFiltersCheckbox.html'

export default angular
  .module('FacetedSideFiltersCheckboxModule', [])
  .component('facetedSideFiltersCheckbox', {
    template,
    bindings: {
      item: '<',
      parent: '<',
      selectedFacetedSideFilters: '<',
      selectedFacetedSideBrands: '<',
    },
    controller($scope, $state, facetedSearchService, utilService) {
      const vm = this
      const { name } = vm.item
      const isBrandFilter = vm.parent.group_display_name === 'Brands'

      $scope.checked = !!(isBrandFilter
        ? vm.selectedFacetedSideBrands[name]
        : vm.selectedFacetedSideFilters[name])

      const selectTagFilter = facetGroupDisplayName => {
        const facetedSideTags = []
        _.each(vm.selectedFacetedSideFilters, (selected, tag) => {
          if (selected) {
            facetedSideTags.push(tag)
          }

          facetedSearchService.setTagCrumbs(selected, tag, 'tags')
          facetedSearchService.setGroupDisplayName(selected, facetGroupDisplayName)
        })

        $state.go('.', {
          tags: facetedSideTags,
          page: null,
          group_display_name: facetedSearchService.tagGroupDisplayNameArray,
        })
      }

      const selectBrandFilter = () => {
        const facetedSideBrands = []
        _.each(vm.selectedFacetedSideBrands, (selected, brand) => {
          if (selected) {
            facetedSideBrands.push(brand)
          }

          facetedSearchService.setTagCrumbs(selected, brand, 'brand_name')
        })

        const key = $state.current.name === 'main.search' ? 'brand_names' : 'brand_name'
        $state.go('.', { [key]: facetedSideBrands, page: null })
      }

      $scope.onCheckboxChange = () => {
        $scope.checked = !$scope.checked

        if (isBrandFilter) {
          vm.selectedFacetedSideBrands[name] = $scope.checked
          selectBrandFilter()
        } else {
          vm.selectedFacetedSideFilters[name] = $scope.checked
          selectTagFilter(vm.parent.group_display_name)
        }

        if ($scope.checked) {
          // focus on previous checked
          utilService.focusNodeWhenInDOM(`#${$scope.createId}`)
        } else {
          // focus on first element, sometimes when unchecked, it is not visible
          utilService.focusNodeWhenInDOM(
            `#facet-section-${vm.parent.group_display_name.replace(' ', '-')} input`
          )
        }
      }

      $scope.createLabelText = `${vm.item.display_name || vm.item.name} (${vm.item.item_count})`

      $scope.createId = `facet-side-filter-checkbox-${vm.item.name?.replace(' ', '-')}`
    },
  })
