import _ from 'lodash'
import angular from 'angular'
import template from './deliveryZipCode.html'

export default angular.module('DeliveryZipCodeModule', []).component('deliveryZipCode', {
  bindings: {
    zipCode: '<',
    onClick: '&',
  },
  template,
  controller($scope, lang) {
    const { add, change } = lang.values
    const hasZipCode = !_.isEmpty(this.zipCode)

    $scope.label = hasZipCode ? change : add
    $scope.handleClick = this.onClick
  },
})
