import angular from 'angular'
import template from '../../components/shared/giftCardModal/giftCardModal.html'

export default angular
  .module('giftCardModalServicesModule', [])
  .service('giftCardModalService', function giftCardModalService($uibModal) {
    /**
     * Opens a gift card modal dialog.
     * */
    this.open = options => {
      return $uibModal.open({
        resolve: {
          giftCardOptions: () => options,
        },
        controller: 'giftCardModalController',
        template,
      }).result
    }
  })
