import isMobileContainerUrl from './isMobileContainerUrl'
import { getBuildConfig } from '../rootConfig'
import urlParser from './urlParser'

const buildConfig = getBuildConfig()

/**
 * Return the html5 version of the url on Cordova.
 *
 * url: `file://<path>/mobile.index.html#/categories`
 * returns: `<webUrl>/categories`
 */

const getHtml5Url = url => {
  const { env } = buildConfig

  if (isMobileContainerUrl(urlParser(url))) {
    const path = url?.split('#')?.[1]
    return `${env.webUrl}${path || ''}`
  }
  return url
}

export default getHtml5Url
