// Helper for keyboard a11y such that toggling focus for keyboard-only users
// is contained within the target node/element

import angular from 'angular'

export default angular
  .module('trapFocusComponentModule', [])
  .directive('trapFocus', ($timeout, utilService) => ({
    link: (scope, element) => {
      $timeout(() => {
        const targetElement = element ? element[0] : null
        const isNativeElement = targetElement ? !!targetElement.addEventListener : false
        if (isNativeElement) {
          const trapFocusOff = utilService.trapFocusOn(targetElement, true)
          element.on('$destroy', () => {
            trapFocusOff()
          })
        }
      }, 0)
    },
  }))
