import angular from 'angular'
import { toast } from 'react-hot-toast'
import UndoItemRemove from '@app/react/components/undoItemRemove/UndoItemRemove'
import OutsideClickHandler from '@app/react/ids/enterprise/molecules/OutsideClickHandler/OutsideClickHandler'

export const undoItemRemoveId = 'undo-item-remove-container'

/**
 * @typedef {Object} _UIProductItem
 * @property {number} quantity
 * @property {StoreProduct} store_product
 * @property {boolean} showDeleteConfirm
 * @property {{options}} product_config
 */

export default angular
  .module('undoRemoveServicesModule', [])
  .service('undoRemove', function undoRemoveService(cartService) {
    const service = this

    /** @param {_UIProductItem} item */
    service.showToast = item => {
      const {
        quantity,
        store_product: { name: itemName },
      } = item
      const toastId = toast(
        // eslint-disable-next-line react/jsx-filename-extension
        <OutsideClickHandler
          onOutsideClick={() => {
            clearTimeout(timerId)
            toast.dismiss(toastId)
          }}
        >
          <UndoItemRemove
            {...{ itemName }}
            id={undoItemRemoveId}
            onUndo={() => {
              clearTimeout(timerId)
              cartService.addItem(item.store_product, quantity).then(() => toast.dismiss(toastId))
            }}
          />
        </OutsideClickHandler>,
        toastOpts
      )
      const timerId = setTimeout(() => {
        toast.dismiss(toastId)
      }, 6000)
    }

    const toastOpts = {
      id: 'undo-product-remove',
      duration: Infinity, // see https://github.com/timolins/react-hot-toast/issues/128 for why we manually hide toast on timeout
      position: 'bottom-center',
      ariaProps: {
        /* on purpose hiding toast for screen readers because non screen reader users can delete an item by accident with a single tap,
        but for screen reader users it is a lot harder to do it by accident because you need to focus on remove item and double tap on it */
        'aria-hidden': true,
      },
    }
  })
