import angular from 'angular'

import {
  // adjustDollarsToCents,
  concatProductTitle,
  getConfigProductTags,
  // getUnclippedCoupons,
  // getProductCellBadgeClassName,
  // getProductPriceLabel,
  // getOverlayTriggerChance,
} from '@app/utils/productHelpers'

const offerBadgeSvg = require('static/images/offer.svg')
/*
 * This controller is used to configure the product cell as we start to move
 * towards the new @instacart/enterprise-product-cell package. As features
 * are migrated, please delete them from this file.
 */
export default angular
  .module('ProductCellReactControllerModule', [])
  .controller(
    'ProductCellReactController',
    function (
      $element,
      $filter,
      $scope,
      addToCartService,
      auth,
      cartService,
      config,
      imageURLService,
      lang,
      mobileService,
      productsService,
      userService
    ) {
      const product = $scope.product || {}
      const cellProps = {}
      const element = $element[0]
      const { index } = $scope

      // Use scope to trigger an update when an item has been changed
      $scope.currentItem = cartService.getItem(product) || {}

      // Flags
      const { isMobile } = mobileService
      const isLoggedIn = auth.user.status !== 'trial'
      // const showAisle = !$scope.config.disableAisles && product.aisle

      /*
       * Generic product cell options
       */
      cellProps.isLoggedIn = isLoggedIn
      cellProps.noBorder = true
      cellProps.noShadow = true
      cellProps.variant = 'default'

      /*
       * Product
       */
      let thumbnailCaption = null

      // Caption - Sponsored By
      if (product.sponsored && product.sponsoredBy) {
        thumbnailCaption = lang.values.sponsoredBy[product.sponsoredBy]
      }

      // Caption - Featured
      if (!thumbnailCaption && $scope.promotedProduct && config.showFeaturedLabel) {
        thumbnailCaption = lang.values.featured
      }

      // Caption - Sponsored
      if (!thumbnailCaption && product.sponsored && config.showSponsoredLabel) {
        thumbnailCaption = lang.values.sponsored
      }

      cellProps.product = {
        id: product.id,
        onClick: () => {
          productsService.cellOnClick({
            location: $scope.location,
            locationValue: $scope.locationValue,
            modal: $scope.productDetailsModalLink,
            product,
            placement: { promoted: $scope.promotedProduct, source: product.sponsoredBy },
          })
        },
        thumbnail: imageURLService.getProductImageURL(product, isMobile ? 'medium' : 'small'),
        title: concatProductTitle(product, lang),
        thumbnailCaption,
      }

      /*
       * Add to cart
       */
      cellProps.addToCart = {
        onAdd: () => {
          addToCartService.onAdd({
            element,
            index,
            location: $scope.location,
            locationValue: $scope.locationValue,
            passToCtrl: {},
            product,
            quantity: $scope.currentItem.quantity || 0,
          })
        },
        onRemove: () => {
          addToCartService.onRemove({
            element,
            index,
            location: $scope.location,
            locationValue: $scope.locationValue,
            product,
            quantity: $scope.currentItem.quantity || 0,
          })
        },
        productCount: $scope.currentItem.quantity || 0,
      }

      /*
       * Aisle
       */
      cellProps.aisle = {
        enabled: !!(!config.disableAisles && product.aisle),
        value: product.aisle?.trim(),
      }

      /*
       * offerBadge || offerPointsBadge
       */
      cellProps.offerBadge = {
        enabled: !!product.offers,
        backgroundSrc: offerBadgeSvg,
      }

      /*
       * price
       */
      cellProps.price = {
        enabled: true,
        loyaltyPricingEnabled: config.enableLoyaltyPricing,
        hasLoyaltyCard: userService.loyaltyCards.length > 0,
        showUom: true,
        product,
      }

      /*
       * priceAlert
       */
      cellProps.priceAlert = {
        enabled:
          !isMobile &&
          !config.disablePriceAlerts &&
          !config.disablePriceAlertsOnProductCells &&
          isLoggedIn,
        initialSelectedState: !!product.price_alert,
      }

      /*
       * rating
       */
      cellProps.rating = {
        enabled: config.userReviews.enabled,
        value: product.product_rating.average_rating,
      }

      /*
       * relatedProduct
       */

      /*
       * salesTag
       */
      const salesTagEnabled =
        product.promo_tag &&
        !!(config.enableLoyaltyPricing && product.promo_tag && product.loyalty_price)
      cellProps.salesTag = {
        enabled: salesTagEnabled,
        ribbonContent: product.promo_tag,
        direction: 'left',
        tooltipContent:
          isMobile || config.showSaleEndDate
            ? ''
            : lang.template(
                lang.values.saleEndDate,
                $filter('apiDate')(lang.values.saleEndDate, 'MMM D')
              ),
      }
      /*
       * saveForLaterList
       */
      cellProps.saveForLaterList = {
        onAdd: () => {},
      }

      /*
       * tags
       */
      const configTags = config.tags
      const productTags = getConfigProductTags(product.tags, configTags)
      cellProps.tags = productTags

      /*
       * unitSize
       */
      cellProps.unitSize = {
        enabled: !!product.size_string,
        value: product.size_string,
      }

      // Set the final product props to scope
      $scope.cellProduct = cellProps

      /*
       * Configurable product
       */
      const update = updatedItem => {
        $scope.currentItem.quantity = updatedItem.quantity || 0

        // Update the product count
        cellProps.addToCart = {
          ...cellProps.addToCart,
          productCount: $scope.currentItem.quantity,
        }

        // Set cell product to newly formatted product
        $scope.cellProduct = cellProps
      }

      // Deregister functions
      const deregisterCartServiceWatcher = $scope.$on(
        'cartService',
        (event, method, newItem = {}) => {
          const cartChange =
            ['add', 'remove'].includes(method) && product.id === newItem?.store_product?.id
          const cartCleared = method === 'clearConfirm'

          if (cartChange || cartCleared) {
            update(newItem)
          }
        }
      )

      $scope.$on('$destroy', () => {
        deregisterCartServiceWatcher()
      })
    }
  )
