import angular from 'angular'
import template from './offerDetailsModal.html'
import OfferDetailsModalControllerModule from './offerDetailsModalController.js'

export default angular
  .module('OfferDetailsModalServiceModule', [OfferDetailsModalControllerModule.name])
  .service(
    'offerDetailsModalService',
    function offerDetailsModalService($uibModal, legacyModalService, uibModalAriaLabelService) {
      const service = this

      service.open = offer => {
        legacyModalService.closeOpenModals()
        $uibModal.open({
          template,
          controller: 'OfferDetailsModalController',
          resolve: {
            ariaLabel: () => {
              uibModalAriaLabelService.setLabel('Offer Details')
            },
            offer: () => offer,
          },
        })
      }
    }
  )
