/*
 * This directive fixes accessibility for the AngularJS Bootstrap Typeahead directive
 * AngularJS Bootstrap Typeahead has two bugs:
 * 1. It places the "aria-expanded" HTML attribute on the input field, when aria-expanded is unnecessary and may cause issues
 * 2. The value it sets it to is just plain wrong.
 *
 * To use:
 *   <input
 *     ...
 *     typeahead-is-open="$ctrl.<state_name>"     // Will make the open/close state available to your controller
 *     typeahead-accessibility                    // Will cause this directive to patch the AngularJS Bootstrap bugs
 *     ...
 *
 * The <state_name> is simply what you expect the open state of the dropdown to be called in your as-controller scope.
 */
import angular from 'angular'

const TYPEAHEAD_DROPDOWN_CSS_CLASS = 'dropdown-menu' // The css class of the dropdown menu generated by AngularJS Bootstrap
const MENU_HIDE_DELAY_MS = 100 // Delay to allow appropriate time for hiding of menu

export default angular
  .module('TypeaheadAccessibilityModule', [])
  .directive('typeaheadAccessibility', function ($parse, $timeout, mobileService) {
    return {
      restrict: 'A',
      link: (scope, element, attr) => {
        const parent = element.parent()
        const typeaheadIsOpenKey = attr.typeaheadIsOpen

        let dropdownMenu = null

        if (!typeaheadIsOpenKey) {
          throw new Error(
            'Please ensure that the "typeahead-is-open" attribute on element with ' +
              `id "${attr.id || '?'}" has a string value to access the scope`
          )
        }

        if (parent) {
          const typeaheadIsOpenGetter = $parse(typeaheadIsOpenKey)

          const accessibilityFix = function (newValue) {
            if (!dropdownMenu) {
              dropdownMenu = element.next(`.${TYPEAHEAD_DROPDOWN_CSS_CLASS}`)
              dropdownMenu.attr('role', 'listbox')
            }
            element.removeAttr('aria-expanded')

            if (!mobileService.isMobile) {
              // as per A360 a11y audit, aria-expanded should only be applied to the input field on desktop
              element.attr('aria-expanded', newValue)
            }
          }

          scope.$watch(() => typeaheadIsOpenGetter(scope), accessibilityFix)

          // The reason for this piece of nasty looking code is because the "typeahead-is-open" attribute does not get
          // toggled on blur - the typehead feature in angular-bootstrap itself is broken!
          // So: If the blur event is followed by a hidden dropdown menu, we consider expanded=false
          element.on('blur', () => {
            $timeout(() => {
              if (dropdownMenu && dropdownMenu.is && !dropdownMenu.is(':visible')) {
                accessibilityFix(false)
              }
            }, MENU_HIDE_DELAY_MS)
          })
        }
      },
    }
  })
