import _ from 'lodash'
import angular from 'angular'
import dayjs from 'dayjs'

import * as storage from '@app/utils/storage'

const SHOPPING_LIST_CACHE_KEYS = {
  CACHED_SHOPPING_LIST: 'cached-shopping-list',
}

const DEFAULT_PRODUCT_IMAGE = require('static/images/no-product-image.jpg')
const DEFAULT_HEADER_IMAGE = require('assets/images/footer-logo.svg')

/**
 *
 * This service manages implements the local caching of the shopping list
 * This has all the function needed from the cart service for the shopping list
 *
 */
export default angular
  .module('ShoppingListCacheModule', [])
  .service(
    'shoppingListCache',
    function shoppingListCache(
      $q,
      $timeout,
      Restangular,
      config,
      configurableProductService,
      utilService
    ) {
      // eslint-disable-next-line import/no-named-as-default-member
      const storageService = storage.getInstance()

      const service = this
      service.cache = null
      service.cart = null
      service.cachePayloadList = []

      /**
       * This will get the cache object
       *
       * @returns { Object } The cache object, the cache object consists of the following values
       *
       * {
       *  cart - {Object} Representing the cart ,
       *  categorizedProductsAndCategories - {Object} Representing the categories of the products,
       *  defaultProductImage - {String} Representing the base64 image of the default product image,
       *  lastModifiedOffline - {String} Representing an ISO formatted datetime,
       * }
       *
       */
      service.getCache = () => {
        if (service.cache) {
          return $q.resolve(service.cache)
        }
        return storageService
          .getItem(SHOPPING_LIST_CACHE_KEYS.CACHED_SHOPPING_LIST)
          .then(cachedShoppingList => {
            if (cachedShoppingList) {
              service.cache = cachedShoppingList
              service.cart = service.cache.cart
              return service.cache
            }
          })
      }
      /**
       * This is needed to mimic the cartservice
       */
      service.fetch = () => service.getCache().then(cache => cache && cache.cart)

      /**
       * This will update the cache. The cache should only be updated directly through this function
       *
       * @param { Object } [cache] An object that will be added to cache NOT overwritten
       *
       * @returns { Object } The cache object, the cache object consists of the following values
       *
       * {
       *  cart - {Object} Representing the cart ,
       *  categorizedProductsAndCategories - {Object} Representing the categories of the products,
       *  defaultProductImage - {String} Representing the base64 image of the default product image,
       *  lastModifiedOffline - {String} Representing an ISO formatted datetime,
       * }
       *
       */
      service.setCache = cache => {
        // Buffer the cache payloads to prevent multiple overriding saves to cache.
        service.cachePayloadList.push(cache)

        $timeout(() => {
          if (!service.cachePayloadList.length) return
          const newCache = service.cachePayloadList.reduce((result, current) => {
            return Object.assign(result, current)
          }, {})
          service.cachePayloadList = []
          // This is needed to remove any functions on the object
          // TODO: maybe move this to the service, how will it affect performance
          const cleanObject = obj => JSON.parse(JSON.stringify(obj))
          return storageService
            .setItem(SHOPPING_LIST_CACHE_KEYS.CACHED_SHOPPING_LIST, cleanObject(newCache))
            .then(updatedCache => {
              service.cache = updatedCache
              service.cart = service.cache.cart
              return service.cache
            })
        }, 1000)
      }

      /**
       * This will save the default product image to the storageservice as base64 image
       */
      const cacheDefaultImages = () => {
        service.getCache().then(cache => {
          if (!cache?.defaultProductImage) {
            utilService.getBase64Image(DEFAULT_PRODUCT_IMAGE).then(noImage64 => {
              service.setCache({ defaultProductImage: noImage64 })
            })
          }
          if (!cache?.defaultHeaderImage) {
            utilService.getBase64Image(DEFAULT_HEADER_IMAGE).then(noHeaderImage64 => {
              service.setCache({ defaultHeaderImage: noHeaderImage64 })
            })
          }
        })
      }
      cacheDefaultImages()

      /**
       * This will compare the last modification on the online version and
       * compare it to last modification of the offline version. If the offline versions
       * last modification was after the last modification of the online version then we
       * overwrite with the offline version.
       *
       * @returns { Promise } Promise with a base64 image as the response
       *
       */
      service.putCacheToCart = () => {
        const getCachedCartModifiedPromise = service
          .getCache()
          .then(({ lastModifiedOffline }) => lastModifiedOffline)
        const userlistEndpoint = Restangular.one('/user_lists/default')
        const getOnlineCartModifiedPromise = userlistEndpoint.get()
        return $q
          .all([getCachedCartModifiedPromise, getOnlineCartModifiedPromise])
          .then(([modifiedOffline, onlineCart]) => {
            if (dayjs(modifiedOffline).isAfter(dayjs(onlineCart.modified))) {
              return userlistEndpoint.customPUT({ items: service.cart.items })
            }
          })
      }

      /**
       * This will set the cart object and the total count of the item to
       * be stored for offline use. We must get the non unique count sent from
       * the cart service so that we are not duplicating the count calculation.
       *
       * @param { Object } [cart] to be saved using the storage service
       * @param { Number } [count] the total count of items in the cart
       *
       * @returns { Object } The cart object that was saved
       *
       */
      service.setCart = cart => {
        return service.setCache({ cart })
      }

      /**
       * This will update the offline cache and update the time of the last made change
       * on the offline cache. All offline modifications should go through this function
       *
       * @param { Object } [cart] to be saved using the storage service
       *
       * @returns { Object } The cart object that was saved
       *
       */
      const setOfflineCache = cache => {
        return service.setCache({ ...cache, lastModifiedOffline: dayjs().toISOString() })
      }

      /**
       * To be able to view the category information offline we need to store it
       *
       * @param { Object } [categories] to be saved using the storage service
       *
       * @returns { Promise } that the categories were saved
       *
       */
      service.setCategories = categorizedProductsAndCategories => {
        return service.setCache({ categorizedProductsAndCategories })
      }

      /**
       * This mimics the 'getCategorizedProductsAndCategories' call from cart service
       *
       * @returns { Promise } with th categories
       *
       */
      service.getCategorizedProductsAndCategories = () => {
        return service.getCache().then(cache => cache && cache.categorizedProductsAndCategories)
      }

      // These function are taken directly from the cart service.
      service.getCustomItems = () =>
        _.filter(service.cart && service.cart.items, { item_type: 'pseudo_product' })

      service.getNonCustomItems = () =>
        _.reject(service.cart && service.cart.items, { item_type: 'pseudo_product' })

      service.getAllProductsByAisle = () => {
        return _.groupBy(service.getNonCustomItems(), item => {
          // Set aisle label to "Aisle Unknown" (or as defined in retailer config) if aisle is null
          return item.store_product && item.store_product.aisle
            ? item.store_product.aisle
            : config.unknownAisleText
        })
      }

      /**
       * This is taken from the update dirty items function in the cart service
       * This is mostly handling the product equality check
       * TODO: product equality should not be handled here and should probably be
       * in some product service
       *
       * @param { Object } [item] the cart item to look for
       *
       * @returns { Number } the index of the product in the cart.items array
       *
       */
      const getProductIndex = (item, list) => {
        const isRegularProductEqual = (itemA, itemB) =>
          itemA.store_product &&
          itemB.store_product &&
          // eslint-disable-next-line eqeqeq
          itemB.store_product.id == itemA.store_product.id
        const isConfigurableProductEqual = (itemA, itemB) =>
          itemA.product_config &&
          itemA.store_product &&
          itemB.product_config &&
          itemB.store_product &&
          itemA.store_product.id === itemB.store_product.id &&
          configurableProductService.isConfigEqual(
            itemA.product_config.options,
            itemB.product_config.options
          )
        // We currently only compare an exact string match, in the future we might want to make it case insensitive
        const isCustomProductEqual = (itemA, itemB) =>
          itemA.pseudo_product &&
          itemB.pseudo_product &&
          itemA.pseudo_product.name === itemB.pseudo_product.name
        return list.findIndex(listItem => {
          return (
            isRegularProductEqual(item, listItem) ||
            isConfigurableProductEqual(item, listItem) ||
            isCustomProductEqual(item, listItem)
          )
        })
      }

      /**
       * Counts total number of items in the cached cart. This was taken from the cart service
       * TODO: write this as a function so it can be reused
       *
       * @param {Object} cart     - A cart object with a items array
       * @return {Promise}        - A promise with the total number of items in a cart
       */
      service.itemCount = () => {
        return service.getCache().then(cache => {
          let itemCount = 0
          cache &&
            cache.cart &&
            cache.cart.items.forEach(item => {
              if (
                item.store_product &&
                (!item.store_product.product_type ||
                  item.store_product.product_type !== 'subscription')
              ) {
                // When the item is a weighted product, just count the number of items to 1.
                if (item.store_product.order_by_weight) {
                  itemCount += 1
                } else {
                  itemCount += item.quantity / item.store_product.ordering_parameters.increment
                }
              }
            })
          return itemCount
        })
      }

      /**
       * This will update an array of existing items. This will make all the updates
       * then set it to the cache. I did it this way to prevent multiple calls to set
       * cache which might have a negative performance impact.
       *
       * @param { Object } [items] An array of items to update
       *
       * @returns { Object } the updated cache
       *
       */
      service.updateItems = items => {
        // This is needed as we save the items in the categorizedProductsAndCategories cache also
        // so they need to be updated there also. I am not sure why but we get the products sorted
        // from the backend and that is the response we are caching, so it creates redundant data
        // so both have to be updated for it to be reflected properley in the ui
        const updateItemInObject = (obj, item) => {
          Object.keys(obj).forEach(key => {
            const productIndex = getProductIndex(item, obj[key])
            if (productIndex > -1) {
              obj[key][productIndex] = item
            }
          })
          return obj
        }

        // This will get the cart and categorizedProductsAndCategories and update the item in both
        // then it will set it to the cache
        return service.getCache().then(({ cart, categorizedProductsAndCategories }) => {
          // eslint-disable-next-line no-restricted-syntax
          for (const item of items) {
            // Replace the item in the cart items arrays if it already exists
            // otherwise add it to the list
            const itemIndex = getProductIndex(item, cart.items)
            if (itemIndex > -1) {
              cart.items[itemIndex] = item
            } else {
              cart.items.push(item)
            }
            // Replace the item in the categorizedProductsAndCategories.productsByCategory object
            categorizedProductsAndCategories.productsByCategory = updateItemInObject(
              categorizedProductsAndCategories.productsByCategory,
              item
            )
          }
          // Set the updated cart and categorizedProductsAndCategories to the cache
          return setOfflineCache({
            cart,
            categorizedProductsAndCategories,
          })
        })
      }

      /**
       * This will update an already existing item
       *
       * @param { Object } [item] the item to update
       *
       * @returns { Object } the updated cache
       *
       */
      service.updateItem = item => service.updateItems([item])

      /**
       * This will remove the items from the cart
       *
       * @param items[] { [object] } The array of items to remove, this will default to the service.cart
       *
       * @returns { Object } the promise that the update has been saved to the cache
       *
       */
      service.removeItems = items => {
        return service
          .updateItems(
            (items || service.cart.items).map(item => {
              return { ...item, quantity: 0 }
            })
          )
          .then(() => {
            // This is needed to mimic the BE response which would update the fields to zero
            service.cart.item_count = 0
            service.cart.discount_total = '0.0'
            service.cart.points.awarded = '0'
            service.cart.product_total = '0.0'
          })
      }

      service.notYetSupported = () => console.warn('This feature is not yet supported')

      service.getItemDescription = item =>
        `${item.store_product.name} ${item.store_product.size_string || ''}`

      // These are all the functions used from the cart service that update the cart
      service.clear = () => service.notYetSupported
      service.sync = () => service.notYetSupported
    }
  )
