import angular from 'angular'
import { ACTIONS } from '@app/angular/constants/recaptcha'

import template from './sharedForgotPassword.html'

export default angular
  .module('SharedForgotPasswordComponentModule', [])
  .directive('sharedForgotPassword', function () {
    return {
      replace: true,
      template,
      controller(
        $attrs,
        $scope,
        config,
        recaptchaService,
        Restangular,
        userAnalytics,
        utilService
      ) {
        // Defines whether the forgot password is on the main flow (context="mainForgotPassword") or the loyalty onboarding flow (context="loyaltyForgotPassword")
        // This is passed in as an attribute when the directive is implemented.

        $scope.forgotPasswordContext = $attrs.context
        $scope.identifier = { email: '' }

        const el = document.getElementById('password-reset-form')

        $scope.inputFocusHelper = function (event) {
          event.preventDefault()
          utilService.getFocusableElements(el)[0].focus()
        }

        // eslint-disable-next-line no-unused-expressions
        el?.addEventListener('touchstart', event => {
          // eslint-disable-next-line no-unused-expressions
          document.getElementById('password-reset-email-error')
            ? $scope.inputFocusHelper(event)
            : null
        })

        $scope.recaptcha = {
          enabled: recaptchaService.isEnabled(),
          token: null,
          action: ACTIONS.PASSWORD_RESET,
        }

        $scope.onRecaptchaSuccess = recaptchaToken => {
          $scope.$apply(() => {
            $scope.recaptcha.token = recaptchaToken
          })
        }

        $scope.onRecaptchaExpired = () => {
          $scope.$apply(() => {
            $scope.recaptcha.token = null
          })
        }

        $scope.reset = function () {
          delete $scope.message
          delete $scope.error

          const passwordResetPayload = {
            email: $scope.identifier.email,
          }

          if ($scope.recaptcha.enabled) {
            if (!$scope.recaptcha.token) {
              $scope.error = $scope.lang.values.recaptchaError
              return
            }
            passwordResetPayload.recaptcha = {
              token: $scope.recaptcha.token,
              siteKey: config.googleRecaptcha?.siteKey,
            }
          }

          $scope.resetting = true
          Restangular.one('user/password_reset')
            .customPUT(passwordResetPayload)
            .then(
              result => {
                delete $scope.identifier
                $scope.message = result.message
                $scope.resetting = false
              },
              response => {
                $scope.resetting = false
                $scope.error = response.data ? response.data.message : response.message
                document.getElementById('password-reset-email').focus()
              }
            )
            .finally(userAnalytics.logUserChangePassword())
        }
      },
    }
  })
