import angular from 'angular'

export default angular
  .module('PriceAlertComponentModule', [])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.account.priceAlerts', {
      url: '/price-alerts',
      views: {
        'content@main.account': {
          template: require('./priceAlerts.html'),
          controller: 'PriceAlertsController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "priceAlertsController", webpackPrefetch: true */ './priceAlertsController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.priceAlerts)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.priceAlerts,
          },
        ],
      },
    })
  })
