import angular from 'angular'

export default angular
  .module('FirstDataPaymentServiceModule', [])

  .config(paymentGatewayProvider => {
    paymentGatewayProvider.registerGateway({
      name: 'FirstData',
      key: 'auth_capture',
      template: require('./firstDataIntegration.html'),
      serviceName: 'firstDataPayment',
      newCheckoutCssClass: 'provider-generic_styling',
    })
  })

  .service('firstDataPayment', function firstDataPayment($q, config, defaultPayments) {
    const service = this

    service.status = {}

    service.checkout = defaultPayments.checkout
  })
