import angular from 'angular'
import OfferDetailsControllerModule from './offerDetailsController' // cannot lazyload - used in multuple places

export default angular
  .module('OffersComponentModule', [OfferDetailsControllerModule.name])
  .config(($ocLazyLoadProvider, $stateProvider) => {
    $stateProvider.state('main.offers', {
      url: '/offers',
      abstract: true,
    })

    $stateProvider.state('main.offers.details', {
      url: '/:offerId',
      resolve: {
        offer($stateParams, offersService) {
          return offersService.getOffer($stateParams.offerId)
        },
      },
      views: {
        'content@': {
          template: require('./offerDetails.html'),
          controller: 'OfferDetailsController',
        },
      },
    })

    $stateProvider.state('main.offers.details.conditions', {
      url: '/condition_products?page',
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "offerCollectionsController", webpackPrefetch: true */ './offerCollectionsController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        customData() {
          return { route: 'condition_products' }
        },
      },
      views: {
        'content@': {
          template: require('./offerCollections.html'),
          controller: 'OfferCollectionsController',
        },
      },
    })

    $stateProvider.state('main.offers.details.rewards', {
      url: '/reward_products?page',
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "offerCollectionsController", webpackPrefetch: true */ './offerCollectionsController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        customData() {
          return { route: 'reward_products' }
        },
      },
      views: {
        'content@': {
          template: require('./offerCollections.html'),
          controller: 'OfferCollectionsController',
        },
      },
    })
  })
