import _ from 'lodash'
import angular from 'angular'
import dayjs from 'dayjs'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { TIMESLOT_FEES } from '@app/angular/constants/timeslots'
import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { hasRxItem } from '@instacart/enterprise-services/shopping-list/_common/utils'
import template from './views/timeslotSelectors.html'

const TIME = 'HH:mm:ss'
const DAY = 'day'
const DAYS = 'days'
const DAY_FORMAT = 'D'
const MONTH_FORMAT = 'MMMM'
const MONTH_SHORT_FORMAT = 'MMM'
const DATE_FORMAT = 'MM-DD-YYYY'
const MOBILE_DAY_FORMAT = 'ddd'
const DESKTOP_DAY_FORMAT = 'dddd'
const SORTED_TIMESLOT_CONFIG = ['timeslot.within_hours', 'timeslot.from_time']

const TIMESLOT_POLYTYPES = {
  ASAP: 'asap',
}

const dayOfTheWeekLabels = (lang, diff, fullLabel, isMobile, customLabels = {}) => {
  const today = customLabels.today !== false ? customLabels.today || lang.values.today : fullLabel
  const tomorrow =
    customLabels.tomorrow !== false ? customLabels.tomorrow || lang.values.tomorrow : fullLabel

  switch (diff) {
    case 0:
      return today
    case -1:
      return !isMobile ? tomorrow : fullLabel
    default:
      return fullLabel
  }
}

export default angular
  .module('TimeslotSelectorsDirectiveModule', [])

  .directive('timeslotSelectors', function () {
    return {
      restrict: 'E',
      scope: {
        /*
         * Properties
         */
        /** [{string}] Important to supply this for new checkout - E.g. "{ <address fields> }" * */
        address: '<?',

        /** {object} E.g. "{ tomorrow: false, isShortDayNames: true, isShortTime: true }" * */
        customLabels: '<',

        /** {string} The fulfillment intent (appears not to be being used though?) * */
        fulfillmentType: '=',

        /** {boolean} true to make the entire row clickable to select, making the button useless but fun * */
        isEntireRowClick: '<',

        /** {boolean} true for a condensed view (i.e. mobile) * */
        isCondensed: '<',

        /** {int} the initial number of timeslots to show, before all should be shown * */
        pagination: '<',

        /** {string} The css class for the top level of this component (useful for styling) * */
        rootLevelCssClass: '@',

        /** {boolean} true for selectable; false for read-only * */
        timeslotsAreSelectable: '=',

        /** {boolean} Whether to filter out timeslots during blackout periods * */
        filterBlackouts: '<',

        /*
         * Callbacks
         */
        /** {function} Called when the a timeslot is selected and succeeds to update the cart (No params & No return) * */
        navigateToNextStep: '&',

        /** {function} Called when the a timeslot is selected, but this fails to update the cart (No params & No return) * */
        navigateToNextStepFailed: '&',

        /** {function} Called when the component begins loading timeslots (No params & No return) * */
        onLoad: '&',

        /** {function} Called when the component has loaded timeslots (No params & No return) * */
        onReady: '&',

        /** {function} Called when the component failed to load timeslots (No params & No return) * */
        onError: '&',

        /** {function} Called after a cart compare confirmation */
        onAfterCartCompareConfirm: '&',
      },
      template,
      controller(
        $filter,
        $rootScope,
        $scope,
        $state,
        $stateParams,
        $timeout,
        analytics,
        analyticsHelper,
        api,
        auth,
        cartService,
        compareCartService,
        config,
        errorModalService,
        lang,
        mobileService,
        modalOptions,
        reservationService,
        shoppingContextService
      ) {
        // Default customLabels
        if (!$scope.customLabels) {
          $scope.customLabels = { tomorrow: false, isShortDayNames: true, isShortTime: true }
        }
        $scope.hasRxItem = cartService.cart ? hasRxItem(cartService.cart.items) : false
        const { isMobile } = mobileService
        const getShopContextIntent = () => {
          const intent = shoppingContextService.selectedFulfillmentType || auth.getContext().intent
          const shoppingModeConfig = config.shoppingSelector[intent]

          return shoppingModeConfig.behaveAs || intent
        }
        const shopContextIntent = getShopContextIntent()
        const isShortDayNames = $scope.customLabels?.isShortDayNames === true
        const deliveryConfig = config.shoppingSelector.delivery
        const willBeFulfilledByInstacart =
          deliveryConfig.fulfilledByInstacart && shopContextIntent === 'delivery'
        const showAvailableTimeslotsOnlyCheckbox =
          deliveryConfig.showAvailableTimeslotsOnlyCheckbox && shopContextIntent === 'delivery'
        const availabilityCheckboxThreshold =
          config.shoppingSelector[shopContextIntent]?.timeslotAvailabilityCheckboxThreshold || 0

        Object.assign(
          $scope,
          {
            $rootScope,
            $state,
            cartService,
            lang,
            reservationService,
            showAvailableTimeslotsOnlyCheckbox,
            availabilityCheckboxThreshold,
            shopContextIntent,
            showDeliveryFee: willBeFulfilledByInstacart,
            willBeFulfilledByInstacart,
            config,
          },
          {
            loading: true,
            loadingReservation: false,
            mobile: mobileService,
            showPickFee: !willBeFulfilledByInstacart,
            showAllTimeslots: !$scope.pagination, // If pagination, default to false. Otherwise true
            isShortTime: !!$scope.customLabels?.isShortTime,
            radioBoxToggleOverride: null,
            availability: {
              showOnlyAvailable: willBeFulfilledByInstacart,
            },
            TIMESLOT_POLYTYPES,
            // Everything below was added to support react time slots
            onTimeSlotSelected: timeSlot => $scope.reserve(0, timeSlot),
            onGoToCart: () => $state.go('main.checkout.cartV2'),
            // This has V2 added because 'selectedTimeSlot' is used in some functions in this component
            selectedTimeSlotV2: reservationService.reservation,
            reactTimeSlotsI18n: {
              availabilityCheckboxLabel: lang.values.availableTimeslotsOnly,
              goToCart: lang.values.goToCart,
              moreTimes: lang.values.moreTimes,
              nextWeek: lang.values.nextWeek,
              previousWeek: lang.values.lastWeek,
              changeWeek: lang.values.changeWeek,
              noTimeslotsAvailableMessage:
                shopContextIntent === 'delivery'
                  ? lang.values.noTimeslotsAvailableDelivery
                  : lang.values.noTimeslotsAvailablePickup,
              aria: {
                title: lang.aria.selectTimeslot,
                daySelection: lang.aria.daySelection,
              },
              day: {
                today: lang.values.today,
              },
              timeSlot: {
                selected: lang.values.selected,
                unavailable: lang.values.unavailable,
                hideDetail: lang.values.hideDetails,
                showDetail: lang.values.showDetails,
                select: lang.values.select,
                deliveryFeeLabel: lang.values.deliveryFees,
                shopFeeLabel: lang.values.pickFees,
                free: lang.values.free,
                noAlcohol: lang.values.noAlcohol,
                highDemand: lang.values.timeslotsHighDemand,
                // TODO(after 2.26): The current string in the config uses a template format that doesn't work with
                // the new lang service, and since we can only have one version of the config this has been commented out.
                // The default string will work for now as no retailer has changed this string.
                // withinHours: lang.values.withinHours,
              },
              rxWarning: lang.values.rx.timeslotWarning,
            },
            timeslots: [],
            onDaySelected: () => {
              analytics.logEvent('fulfillment timeslot day of week button', 'click')
            },
            onWeekChanged: ({ currentPage, newPage }) => {
              if (currentPage - newPage > 0) {
                analytics.logEvent('fulfillment timeslot next week button', 'click')
              } else if (currentPage - newPage < 0) {
                analytics.logEvent('fulfillment timeslot last week button', 'click')
              }
            },
          }
        )

        let timeslots
        let lastDay
        let firstDay
        let selectedTimeslot

        this.$onInit = () => {
          this.isCondensed = this.isCondensed ?? false
        }

        const toggleGetTimeslotComplete = value => ($scope.getTimeslotsComplete = value)

        const processTimeSlots = (timeslots = []) => {
          timeslots.forEach(timeslot => {
            const day = dayjs(timeslot.fulfillment_date).startOf(DAY)
            if (!lastDay || day.unix() > lastDay.unix()) {
              lastDay = day
            }
            if (!firstDay || day.unix() < firstDay.unix()) {
              firstDay = day
            }
          })

          // Sort timeslots
          const sortedTimeslots = _.sortBy(timeslots, SORTED_TIMESLOT_CONFIG)

          // Generate a list of days grouped by week
          $scope.days = []
          const asapTimeslot = timeslots.find(
            timeslot => timeslot.timeslot.polytype === TIMESLOT_POLYTYPES.ASAP
          )
          if (asapTimeslot) {
            const asapDay = dayjs(asapTimeslot.fulfillment_date)
            $scope.days.push({
              dayOfWeek: lang.values.fastest,
              dayOfMonth: lang.values.possible,
              month: '',
              monthShort: '',
              date: TIMESLOT_POLYTYPES.ASAP,
              daysSinceStart: asapDay.diff(firstDay, DAYS),
            })
          }
          for (let day = dayjs(firstDay); day.diff(lastDay, DAYS) <= 0; day = day.add(1, DAYS)) {
            const diff = dayjs().startOf(DAY).diff(day, DAYS)
            const format = day.format(
              isMobile || isShortDayNames ? MOBILE_DAY_FORMAT : DESKTOP_DAY_FORMAT
            )

            $scope.days.push({
              dayOfWeek: dayOfTheWeekLabels(lang, diff, format, isMobile, $scope.customLabels),
              dayOfMonth: day.format(DAY_FORMAT),
              month: day.format(MONTH_FORMAT),
              monthShort: day.format(MONTH_SHORT_FORMAT),
              date: day.format(DATE_FORMAT),
              daysSinceStart: day.diff(firstDay, DAYS),
            })
          }

          // Group days by week
          const dayOrd = dayObj => Math.floor(dayObj.daysSinceStart / 7)
          $scope.daysByWeek = $scope.days.reduce((arr, dayObj) => {
            const day = dayOrd(dayObj)
            // eslint-disable-next-line no-multi-assign
            const weekArr = (arr[day] = arr[day] || [])
            dayObj.week = arr.length - 1
            weekArr.push(dayObj)
            return arr
          }, [])

          // Group timeslots by day
          const dateStr = timeslot => dayjs(timeslot.fulfillment_date).format(DATE_FORMAT)
          $scope.timeslotsByDay = sortedTimeslots.reduce((dayObject, timeslot) => {
            if (timeslot.timeslot.polytype === TIMESLOT_POLYTYPES.ASAP) {
              dayObject[TIMESLOT_POLYTYPES.ASAP] = [timeslot]
            } else {
              const time = dateStr(timeslot)
              // eslint-disable-next-line no-multi-assign
              const day = (dayObject[time] = dayObject[time] || [])
              day.push(timeslot)
            }

            const { from_time, to_time } = timeslot.timeslot
            if (dayjs(from_time, TIME).minute() || dayjs(to_time, TIME).minute()) {
              $scope.isShortTime = false // If there are any times with minutes, we turn off isShortTime
            }

            return dayObject
          }, {})

          $scope.timeslotsByDayPaginated = $scope.pagination
          $scope.currentWeek = 0 // Keep track of which week we're viewing
          $scope.reservationWeek = $scope.days[0].week
          selectInitialDate()
        }

        /**
         * This method sets the initial state of the component by
         * selecting the day, week and expanding the pagination
         * if the user timeslot reservation is hidden
         *
         * @returns {undefined}
         */
        const selectInitialDate = () => {
          const { reservation } = reservationService
          const reservationDate = reservation?.fulfillment_date
          const reservationTimeslot = reservation?.timeslot

          if (reservationDate) {
            const date = dayjs(reservationDate).startOf(DAY).format(DATE_FORMAT)
            const dayInList = $scope.days.find(day => day.date === date) || $scope.days[0]
            const dayTimeslots = $scope.timeslotsByDay[dayInList.date]

            $scope.reservationWeek = dayInList.week

            if (dayTimeslots && reservationTimeslot) {
              const timeslotIndex = dayTimeslots.findIndex(
                time => time.timeslot.id === reservationTimeslot.id
              )

              // Show all timeslots if the reservation timeslot is hidden
              if (timeslotIndex + 1 > $scope.pagination) {
                $scope.showAllTimeslots = true
              }
            }

            $scope.selectDay(dayInList)
            $scope.currentWeek = dayInList.week

            if (isMobile) {
              $timeout(() => {
                const scrollableEl = document.getElementById('scrollable-timeslot-days')
                const dayEl = document.getElementById(
                  `reservation-select-day-${dayInList.daysSinceStart}`
                )

                // eslint-disable-next-line no-unused-expressions
                scrollableEl?.scroll(dayEl?.offsetLeft || 0, 0)
              }, 0)
            }
          } else {
            $scope.selectDay($scope.days[0])
          }
        }

        const getTimeslots = args => {
          if ($scope.onLoad) {
            $scope.onLoad()
          }

          const payload = {
            fulfillment_type: $scope.shopContextIntent,
            filter_product_blackouts_timeslots: $scope.filterBlackouts,
            ...args,
          }

          payload.cart_id = cartService.getCartId(cartService.getCurrentCartType())
          payload.user_timezone = Intl.DateTimeFormat().resolvedOptions().timeZone

          return api
            .getTimeSlotsList(payload)
            .then(response => {
              $scope.timeslots = response
              analyticsHelper.availabilityViewEvent(response)
              return (timeslots = response)
            })
            .then(() => toggleGetTimeslotComplete(false))
            .then(() => processTimeSlots(timeslots))
            .then(() => toggleGetTimeslotComplete(true))
            .then(() => $scope.onReady && $scope.onReady())
            .catch(response => {
              $scope.onError && $scope.onError({ response })
              if (response.data.error === HTTP_ERRORS.ALCOHOL_FULFILLMENT_ERROR) {
                $scope.reactTimeSlotsI18n.noTimeslotsAvailableMessage = response.data.message
              }
            })
            .finally(() => {
              $scope.loading = false
            })
        }

        const processReserve = newTimeslot => {
          const { fulfillment_date, timeslot } = newTimeslot
          const payload = newTimeslot.plain ? { ...newTimeslot.plain() } : { ...newTimeslot }

          const cart_id = cartService.getCartId(cartService.getCurrentCartType())
          if (cart_id) {
            payload.cart_id = cart_id
          }

          return reservationService.makeReservation(payload).then(() => {
            // In the checkout page, only log edit_timeslot events and not select_timeslot.
            if ($scope.$state.current.name !== 'main.checkout.checkoutPage') {
              analytics.logSegmentEvent('availability.select_timeslot', {
                fulfillment_date,
                timeslot,
              })
            }
            return cartService.fetch().then(() => $scope.navigateToNextStep())
          })
        }

        const handleProcessReservationError = response => {
          const logReserveError = () =>
            $scope.navigateToNextStepFailed && $scope.navigateToNextStepFailed()

          // Handles expired timeslot selection
          if (response && response.status === HTTP_ERRORS.FORBIDDEN) {
            getTimeslots()
              .then(() => ($scope.error = lang.values.expiredTimeslotError))
              .then(logReserveError)
          } else {
            $scope.error = response?.data || response
            logReserveError()
          }

          $scope.onError && $scope.onError({ response })
        }

        $scope.toggleUnavailableReason = timeslot => {
          timeslot.showUnavailableReason = !timeslot.showUnavailableReason
        }

        // Select a particular day and the first timeslot for that day in the select list

        $scope.selectDay = day => {
          $scope.selectedDay = day
          selectedTimeslot = null

          const el = document.querySelector('.timeslots tbody tr')

          // For keyboard users (accessibility), set the focus to the timeslots when a day is selected
          if (el) {
            el.focus()
          }
        }

        $scope.resetSelectedTimeslot = () => {
          selectedTimeslot = null
        }

        $scope.rowClick = ($index, timeslot) => {
          if (!$scope.timeslotsAreSelectable || timeslot.timeslot.unavailable) return

          if ($scope.isEntireRowClick) {
            $scope.reserve($index, timeslot)
          }
        }

        // Reserve the selected timeslot - Check if inventory or price of products have changed.
        $scope.reserve = (selectedIndex, timeslot, isSkip) => {
          if (isSkip || timeslot.timeslot.unavailable) return

          delete $scope.error
          selectedTimeslot = timeslot

          const timeslotStartTime = `${selectedTimeslot.fulfillment_date} ${selectedTimeslot.timeslot.from_time}`
          $scope.selectedTimeslotIndex = selectedIndex
          $scope.loadingReservation = true
          $scope.radioBoxToggleOverride = selectedTimeslot.timeslot.id

          compareCartService
            .compareCart(true, 'timeslot', {
              timeslot: selectedTimeslot.timeslot,
              fulfillment_date: timeslotStartTime,
            })
            .then($scope.onAfterCartCompareConfirm)
            .then(() => processReserve(selectedTimeslot))
            .catch(handleProcessReservationError)
            .finally(() => {
              $scope.loadingReservation = false
              $scope.radioBoxToggleOverride = null
              $scope.resetSelectedTimeslot()
            })
        }

        $scope.updateTimeslotFee = timeslot => {
          const currentValue =
            timeslot[TIMESLOT_FEES[timeslot.source][shopContextIntent] || TIMESLOT_FEES.DEFAULT]
          const { enabled, value, text } = config.shopperFeeTextChange

          if (timeslot.is_free) {
            return lang.values.free
          }

          return enabled && currentValue === value ? text : $filter('currency')(currentValue)
        }

        $scope.isToggled = timeslotItem => {
          const reservation = reservationService.reservation?.timeslot
          const timeslot = timeslotItem?.timeslot

          /**
           * Weeks share timeslots with same IDs, this check prevents
           * the radio input from checking if the timeslot ID is the same as the
           * user reservation but from a different week
           */
          if ($scope.selectedDay.week !== $scope.reservationWeek) {
            return false
          }

          if ($scope.radioBoxToggleOverride) {
            return $scope.radioBoxToggleOverride === timeslot?.id
          }

          if (reservation && timeslot) {
            return (
              reservation.id === timeslot.id ||
              (reservation.ext_id && reservation.ext_id === timeslot.ext_id)
            )
          }

          return false
        }

        $scope.getUnavailabilityReasonString = timeslot =>
          timeslot.timeslot.unavailability_reasons.join('<br /><br />')

        $scope.toggleShowMore = () => {
          $scope.showAllTimeslots = !$scope.showAllTimeslots
        }

        $scope.getFeeLabel = timeslot => {
          if (timeslot.unavailable && !$scope.timeslotsAreSelectable) {
            return lang.values.unavailable
          }

          return shopContextIntent === SHOPPING_MODES.DELIVERY
            ? lang.values.deliveryFees
            : lang.values.pickFees
        }

        let getTimeslotArgs = {}

        // If not on the checkout page, pass above_threshold as True.
        // Otherwise, pass the street_address and delivery postal code,
        // and above_threshold as False
        if ($scope.$state.current.name !== 'main.checkout.checkoutPage') {
          getTimeslotArgs = {
            above_threshold: true,
          }
        } else {
          // TODO Remove $scope.address once CRM for HEI and CCA is fixed to sync address correctly
          const address = $scope.address || auth.getContext()
          getTimeslotArgs = {
            above_threshold: false,
            street_address: address?.address1 || address?.street_address,
            deliveryPostalCode: address?.postal_code || address?.deliveryPostalCode,
          }
        }

        getTimeslots(getTimeslotArgs)
      },
    }
  })
