import angular from 'angular'
import { captureMessage } from '@sentry/browser'
import { user } from '@instacart/enterprise-services'

export default angular
  .module('profileServicesModule', [])
  .service('profileService', function (ngPromise) {
    const service = this
    const getProfile = ngPromise.wrapper(user.api.user.getProfile)

    service.profile = {}

    /**
     * This service provides a way to cache profile object as angular service
     * to reduce redundant call to /profile endpoint
     */
    service.fetchProfile = function () {
      service.fetchProfilePromise = getProfile()
        .then(profile => {
          service.profile = profile || {}

          return service.profile
        })
        .catch(error => {
          captureMessage(
            `Error fetching profile. Status: "${error?.status}". Message: "${
              error?.message || error
            }"`
          )
        })

      return service.fetchProfilePromise
    }

    service.getProfile = () => {
      return service.fetchProfilePromise || service.fetchProfile()
    }
  })
