// Removes any attribute name passed into a scoped array via `sanitize-uib` variable.
// Due to our use of `angular-ui-bootstrap`, we unfortunately need to "hijack" some of
// the default functionality of its `dropdown` plugin, which manifests by
// uibDropdown having a default scope watcher for `isOpen` that sets `aria-expanded`.
// Along with `aria-haspopup`, it must be removed for a11y reasons. The addToList component
// will pass ['aria-haspopup','aria-expanded'] which gets removed by this directive

import angular from 'angular'

export default angular.module('SanitizeUibModule', []).directive('sanitizeUib', function () {
  return {
    scope: {
      sanitizeUib: '=',
    },
    require: '?^uibDropdown',
    link: (scope, el, attr, dropdownCtrl) => {
      const removeAttributes = () =>
        scope.sanitizeUib.forEach(attribute => {
          el[0].removeAttribute(attribute)
        })

      scope.$watch(dropdownCtrl.isOpen, () => removeAttributes())
    },
  }
})
