import _ from 'lodash'
import angular from 'angular'
import FacetedFilterModalControllerModule from './facetedFilterModalController.js'
import FacetedFilterModalLineItemModule from './facetedFilterModalLineItem.js'

export default angular
  .module('FacetedFilterModalModule', [
    FacetedFilterModalControllerModule.name,
    FacetedFilterModalLineItemModule.name,
  ])
  .component('facetedFilterModal', {
    bindings: {
      filterModalOptions: '<',
      dismiss: '<',
      close: '<',
    },
    controller($scope, $state, $stateParams, facetedSearchService, lang) {
      const vm = this
      // Ones we definitely need
      vm.lang = lang
      vm.selectedBrands = []
      vm.selectedFacets = []
      vm.searchTerm = vm.filterModalOptions.searchTerm || $stateParams.search_term || null

      vm.sortOptions = vm.filterModalOptions.sortOptions
      vm.coreFacets = vm.filterModalOptions.tagOptions
      vm.secondaryFacets = facetedSearchService.categorizedFacetsObject.sideFilters
      vm.hideFacets = facetedSearchService.hideFacets

      const currentSort = vm.filterModalOptions.sort || vm.filterModalOptions.defaultSort || null
      vm.sort = currentSort

      vm.showSort = vm.filterModalOptions.showSort

      vm.content = vm.filterModalOptions.content
      // See All/Minimize functionality
      vm.baseNumberShown = 10
      vm.facetShowMore = _.map(
        facetedSearchService.categorizedFacetsObject.sideFilters,
        () => vm.baseNumberShown
      )

      vm.showOrHideMore = (index, totalNumber) => {
        vm.facetShowMore[index] = totalNumber || vm.baseNumberShown
      }

      // "Sort by selection" and "previously selected" initialization
      vm.selectedBrandsCheckboxObject = {}
      if ($stateParams.brand_names || $stateParams.brand_name) {
        const brandNamesParamArray = $stateParams.brand_names || $stateParams.brand_name

        if (Array.isArray(brandNamesParamArray)) {
          brandNamesParamArray.forEach(brandName => {
            vm.selectedBrands.push(brandName)
            vm.selectedBrandsCheckboxObject[brandName] = true
          })
        } else {
          vm.selectedBrands.push(brandNamesParamArray)
          vm.selectedBrandsCheckboxObject[brandNamesParamArray] = true
        }
      }

      vm.selectedFacetsCheckboxObject = {}
      if ($stateParams.tags) {
        if (_.isArray($stateParams.tags)) {
          _.each($stateParams.tags, tag => {
            vm.selectedFacets.push(tag)
            vm.selectedFacetsCheckboxObject[tag] = true
          })
        } else {
          vm.selectedFacets.push($stateParams.tags)
          vm.selectedFacetsCheckboxObject[$stateParams.tags] = true
        }
      }

      const areTagsSameAsOriginal = (originalSelectedTags, currentSelectedTags) => {
        // originalSelectedTags can be either undefined | string | string[]
        if (Array.isArray(originalSelectedTags)) {
          // string[] (more than one filters selected)
          return (
            originalSelectedTags.length === currentSelectedTags.length &&
            _.isEqual(originalSelectedTags, currentSelectedTags)
          )
        }
        if (typeof originalSelectedTags === 'string') {
          // string (only one filter selected)
          return (
            currentSelectedTags.length === 1 && currentSelectedTags.includes(originalSelectedTags)
          )
        }
        // undefined (no filter selected)
        return currentSelectedTags.length === 0
      }

      vm.isApplyButtonDisabled = true
      const updateApplyButton = () => {
        const originalSelectedFacets = $stateParams.tags
        const originalSelectedBrands = $stateParams.brand_names || $stateParams.brand_name

        vm.isApplyButtonDisabled =
          areTagsSameAsOriginal(originalSelectedFacets, vm.selectedFacets) &&
          areTagsSameAsOriginal(originalSelectedBrands, vm.selectedBrands) &&
          currentSort === vm.sort
      }
      updateApplyButton()

      // A toggle function for adding tags to our selected tags
      vm.toggleTag = id => {
        if (_.includes(vm.selectedFacets, id)) {
          _.remove(vm.selectedFacets, currentID => {
            return currentID === id
          })
        } else {
          vm.selectedFacets.push(id)
        }
        updateApplyButton()
      }

      // A toggle functions for adding brands to the selected brands
      vm.toggleBrand = id => {
        if (_.includes(vm.selectedBrands, id)) {
          _.remove(vm.selectedBrands, currentID => {
            return currentID === id
          })
        } else {
          vm.selectedBrands.push(id)
          facetedSearchService.setTagCrumbs(true, vm.selectedBrands, 'brand_name')
        }
        updateApplyButton()
      }

      // Reset the filters to their defaults
      vm.reset = function () {
        vm.selectedBrands = []
        vm.selectedFacets = []
        vm.selectedBrandsCheckboxObject = {}
        vm.selectedFacetsCheckboxObject = {}
        updateApplyButton()
      }

      // Apply the filters!
      vm.apply = function () {
        const key = $state.current.name === 'main.search' ? 'brand_names' : 'brand_name'

        $state.go('.', {
          content: vm.content,
          sort: vm.sort,
          tags: vm.selectedFacets.length > 0 ? vm.selectedFacets : null,
          [key]: vm.selectedBrands.length > 0 ? vm.selectedBrands : null,
          search_term: vm.searchTerm,
        })
      }

      // This has been added to support keyboard accessible selecting of brands and tags from the filter
      // list. This was a quick solution, using tabindex="0" to keyboard focus the li elements within this component,
      // and this function handles updating the selection state (toggleBrand()) and updating the child faceted-filter-modal-line-item component's
      // checkbox UI elements' value.
      vm.keyboardToggleOption = (id, areBrands) => {
        if (areBrands) {
          vm.selectedBrandsCheckboxObject[id] = !vm.selectedBrandsCheckboxObject[id]
          vm.toggleBrand(id)
        } else {
          vm.selectedFacetsCheckboxObject[id] = !vm.selectedFacetsCheckboxObject[id]
          vm.toggleTag(id)
        }
      }

      $scope.$watch(() => vm.sort, updateApplyButton)
    },
    template: `
      <div class="filter-modal modal-container" data-test="filter-modal">
        <div class="modal-header">
          <button ng-click="$ctrl.dismiss()" class="close" analytics-click="['filter modal close button', 'click']" aria-label="{{::$ctrl.lang.aria.modalClose}}">
            <span class="icon-delete" aria-hidden="true"></span>
          </button>
          <h1 class="screen-reader-text">{{::$ctrl.lang.values.filters}}</h1>
          <button ng-click="$ctrl.reset()" ng-disabled="$ctrl.selectedFacets.length + $ctrl.selectedBrands.length === 0" class="reset" analytics-click="['filter modal reset button', 'click']">
            {{::$ctrl.lang.values.reset}}
            <span ng-if="$ctrl.selectedFacets.length + $ctrl.selectedBrands.length > 0">({{$ctrl.selectedFacets.length + $ctrl.selectedBrands.length}})</span>
          </button>
        </div>

        <div class="filter-modal-content modal-content">

          <fieldset ng-if="$ctrl.sortOptions && $ctrl.sortOptions.length && $ctrl.showSort">
            <legend>
              <h2>{{::$ctrl.lang.values.sortBy}}</h2>
            </legend>
            <ul class="sort">
              <li ng-repeat="sortOption in $ctrl.sortOptions" class="sort-option">
                <input ng-model="$ctrl.sort" type="radio" name="sortOption" id="tag-{{::sortOption.value}}" ng-value="sortOption.value" class="radio" aria-current="{{sortOption.value === $ctrl.sort ? true : false}}">
                <label for="tag-{{::sortOption.value}}" class="radio-button">
                  <span>{{sortOption.name}}</span>
                </label>
              </li>
            </ul>
          </fieldset>

          <fieldset ng-if="($ctrl.coreFacets && $ctrl.coreFacets.length > 0)">
            <legend>
              <h2>{{::$ctrl.lang.values.filters}}</h2>
            </legend>
            <ul class="tags list">
              <li ng-repeat="facetOption in $ctrl.coreFacets" class="tag-option tag {{facetOption.value}} list-item">
                <faceted-filter-modal-line-item tag-option="facetOption" selected-checkbox-object="$ctrl.selectedFacetsCheckboxObject" toggle-option="$ctrl.toggleTag(id)"></faceted-filter-modal-line-item>
              </li>
            </ul>
          </fieldset>

          <fieldset ng-if="!$ctrl.hideFacets" ng-repeat="facet in $ctrl.secondaryFacets">
            <legend>
              <h2>{{facet.group_display_name}}</h2>
            </legend>
            <ul class="tags list">
              <li ng-repeat="facetOption in facet.items | sortBySelected: 'name':$ctrl.selectedBrandsCheckboxObject:$ctrl.selectedFacetsCheckboxObject | limitTo: $ctrl.facetShowMore[$index] track by facetOption.name" class="tag-option tag {{facetOption.value || facetOption.name}} list-item">
                <faceted-filter-modal-line-item tag-option="facetOption" selected-checkbox-object="$ctrl.selectedBrandsCheckboxObject" toggle-option="$ctrl.toggleBrand(id)"  ng-if="facet.group_display_name === 'Brands'"></faceted-filter-modal-line-item>
                <faceted-filter-modal-line-item tag-option="facetOption" selected-checkbox-object="$ctrl.selectedFacetsCheckboxObject" toggle-option="$ctrl.toggleTag(id)" ng-if="facet.group_display_name !== 'Brands'"></faceted-filter-modal-line-item>
              </li>
            </ul>
            <a ng-click="$ctrl.showOrHideMore($index, facet.items.length)" ng-if="facet.items.length > $ctrl.facetShowMore[$index]"  aria-expanded="false">{{::$ctrl.lang.values.seeAll}} ({{facet.items.length}})</a>
            <a ng-click="$ctrl.showOrHideMore($index)" ng-if="facet.items.length === $ctrl.facetShowMore[$index] && facet.items.length > $ctrl.baseNumberShown" aria-expanded="true">{{::$ctrl.lang.values.seeAll}}</a>
          </fieldset>

        </div>

        <button ng-click="$ctrl.apply()" ng-disabled="$ctrl.isApplyButtonDisabled" class="button mobile-bottom footer" analytics-click="['filter modal apply button', 'click']" data-test="mobile-apply-filters-button">{{::$ctrl.lang.values.applyFilters}}</button>
      </div>
    `,
  })
