import angular from 'angular'

export default angular
  .module('ExternalPageComponentModule', [])
  .config(($ocLazyLoadProvider, $stateProvider) => {
    $stateProvider.state('main.externalPage', {
      url: '/external/{id}',
      views: {
        'content@': {
          template: require('./externalPage.html'),
          controller: 'ExternalPageController',
        },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          return import(
            /* webpackChunkName: "externalPageController", webpackPrefetch: true */ './externalPageController'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
      },
    })
  })
