import { config as configService } from '@instacart/enterprise-services'
import { windowManager } from '@instacart/enterprise-services-cordova'
import { OpenAuthBrowserOptions } from '@instacart/enterprise-services-cordova/window-manager'
import isMobileApp from '@app/utils/isMobileApp'
import { AUTH_ACTIONS } from '@app/angular/constants/authActions'
import { SSO_PROVIDERS } from '@app/angular/constants/ssoProviders'
import handleExternalAuth from '@app/services/externalAuth/handleExternalAuth'
import objectToQueryString from '@app/utils/objectToQueryString'

export default class ExternalOAuth2 {
  openUrl(
    link: string,
    {
      closeOnHome,
      shouldResolve,
    }: Partial<Pick<OpenAuthBrowserOptions, 'closeOnHome' | 'shouldResolve'>> = {}
  ): Promise<void> {
    if (isMobileApp()) {
      return windowManager.openAuthUrl(link, { closeOnHome, shouldResolve })
    }

    window.open(link, '_self')

    // Hold the promise chain else it continues and reloads the page.
    return new Promise(() => {})
  }

  initLogin() {
    return handleExternalAuth(SSO_PROVIDERS.EXTERNAL_OAUTH2_IDP)
  }

  initLogout() {
    const config = configService.selectors.getConfig()
    const externalOauthConfig = config.externalOAuth2?.serviceParams
    const logoutUrl = externalOauthConfig?.logoutUrl as string
    const clientId = externalOauthConfig?.clientId as string
    const queryParamRedirectUriKey = externalOauthConfig?.logoutRequestParams
      ?.postLogoutRedirectUriKey as string
    const queryParamRedirectUriValue = externalOauthConfig?.logoutRequestParams
      ?.postLogoutRedirectUriValue as string

    // return and invoke logout URL if there are no complementary request params with it
    if (!queryParamRedirectUriValue) {
      return this.openUrl(logoutUrl, { closeOnHome: true, shouldResolve: true })
    }

    // construct url query and then invoke logout with appropriate request params
    const urlQuery = objectToQueryString({
      client_id: clientId,
      [queryParamRedirectUriKey]: queryParamRedirectUriValue,
    })
    return this.openUrl(`${logoutUrl}?${urlQuery}`, { closeOnHome: true, shouldResolve: true })
  }

  init(view: AUTH_ACTIONS) {
    switch (view) {
      case AUTH_ACTIONS.LOGIN:
        return this.initLogin()
      case AUTH_ACTIONS.LOGOUT:
        return this.initLogout()
      default:
        return Promise.resolve()
    }
  }
}
