import _ from 'lodash'
import angular from 'angular'
import { AUTH_ACTIONS } from '@app/angular/constants/authActions'
import { auth as authService } from '@instacart/enterprise-services'

export default angular
  .module('GigyaServiceModule', [])
  .service(
    'gigyaService',
    function gigyaService(
      $q,
      $rootScope,
      $state,
      $uibModalStack,
      $window,
      anonymousIdService,
      auth,
      config,
      errorModalService,
      mobileService
    ) {
      const service = this
      const isMobileApp = mobileService.isMobileApp()
      const sdkLoadedDefer = $q.defer()
      service.sdkLoaded = sdkLoadedDefer.promise

      // Run Block is executed after all of the services have been configured and the injector has been created
      // https://docs.angularjs.org/guide/module#run-blocks
      if (config.gigya && config.gigya.enabled && !isMobileApp) {
        const script = document.createElement('script')
        script.type = 'text/javascript'
        script.src = config.gigya.src + config.gigya.APIKey
        document.body.appendChild(script)

        if (script.readyState) {
          // IE
          script.onreadystatechange = () => {
            // eslint-disable-next-line eqeqeq
            // eslint-disable-next-line eqeqeq
            if (script.readyState == 'loaded' || script.readyState == 'complete') {
              script.onreadystatechange = null
              $window.gigya = window.gigya
              sdkLoadedDefer.resolve()
            }
          }
        } else {
          script.onload = function () {
            $window.gigya = window.gigya
            sdkLoadedDefer.resolve()
          }
        }
      }

      const handleCheckoutAddressUpdate = (_gigyaResponse, userPromise) => {
        // Patching the profile busts the cache on the backend
        return auth.updateProfile({}).then(user => {
          userPromise.resolve(user)
          return $state.reload()
        })
      }

      const handleGigyaSubmit = (gigyaResponse, userPromise) => {
        // For some reason the plugin is returning the gigyaResponse.resonse as a string
        // so we have to parse it
        if (isMobileApp) {
          gigyaResponse.response = JSON.parse(gigyaResponse.response)
        }

        const isLoginScreen = gigyaResponse.screen === 'gigya-login-screen'
        const isRegistrationScreen = gigyaResponse.screen === 'gigya-register-screen'

        const validLoginRegisterResponse =
          gigyaResponse.response.errorCode === 0 &&
          gigyaResponse.response.UID &&
          gigyaResponse.response.UIDSignature &&
          gigyaResponse.response.signatureTimestamp

        // Check if a user is submitting a Forgot/Reset Password request, in which case do not attempt a login/register with `auth/external`
        if (gigyaResponse.screen === 'update-delivery-address') {
          return handleCheckoutAddressUpdate(gigyaResponse, userPromise)
        }
        if (
          gigyaResponse.screen === 'gigya-forgot-password-screen' ||
          gigyaResponse.screen === 'gigya-reset-password-screen'
        ) {
          if ($uibModalStack.getTop()) {
            $uibModalStack.dismissAll()
          }
          return userPromise.resolve()
        }
        if (
          gigyaResponse.screen === 'gigya-change-password-screen' &&
          gigyaResponse.response.errorCode === 0
        ) {
          // Close screen-set and show success message after this is complete
          if (!isMobileApp) {
            gigya.accounts.hideScreenSet({
              screenSet: config.gigya.screenSets.ProfileUpdate,
            })
          }

          userPromise.resolve()
          return errorModalService.open(
            'Password changed successfully.',
            null,
            config.lang.close,
            'Success'
          )
        }
        if (
          gigyaResponse.screen === 'gigya-update-profile-screen' &&
          gigyaResponse.response.errorCode === 0
        ) {
          // Patching the profile busts the cache on the backend
          return auth
            .updateProfile({})
            .then(user => {
              userPromise.resolve(user)
              return errorModalService.open(
                'Profile updated successfully.',
                config.lang.close,
                null,
                'Success'
              )
            })
            .then(() => {
              // If it's native, go to the home page after editing is done.
              if (isMobileApp) {
                $state.go('main.home')
              }
            })
        }
        if (
          validLoginRegisterResponse &&
          (isLoginScreen ||
            (isRegistrationScreen && config.gigya.enableRegistrationLogsIn === true))
        ) {
          let submitAction = ''

          if (isLoginScreen) {
            submitAction = 'login'
          } else if (isRegistrationScreen) {
            submitAction = 'registration'
          }

          const loginPayload = {
            identifier_polytype: 'gigya',
            identifier_data: {
              uid: gigyaResponse.response.UID,
              uid_signature: gigyaResponse.response.UIDSignature,
              signature_timestamp: gigyaResponse.response.signatureTimestamp,
              action: submitAction,
            },
          }

          $rootScope.$apply(rootScope => (rootScope.isAppLoading = true))
          return authService.promiseActions
            .loginExternal(loginPayload)
            .then(
              () => auth.handlePostLogin().then(user => userPromise.resolve(user)),
              () => {
                // Do nothing. This is to prevent two modals from showing.
              }
            )
            .catch(loginResponse => {
              return errorModalService.open(
                loginResponse.message || config.lang.defaultErrorMessage,
                'Back To Home'
              )
            })
            .finally(() => {
              $rootScope.isAppLoading = false
              if ($uibModalStack.getTop()) {
                $uibModalStack.dismissAll()
              }
            })
        }
      }

      const handleGigyaHide = hideResponse => {
        // Redirect user to homepage if user is `trial` and there is either no current state or state `data.requireRegistered`
        if (
          hideResponse.reason === 'canceled' &&
          // eslint-disable-next-line eqeqeq
          auth.user.status == 'trial' &&
          ($state.current.name === '' || $state.data.requireRegistered)
        ) {
          $state.go('main.home')
        }
      }

      service.gigyaLogin = () => {
        const userPromise = $q.defer()

        if (isMobileApp) {
          gigyaClient.showPlugin(
            'accounts.screenSet',
            {
              screenSet: config.gigya.screenSets.registrationLogin,
            },
            {
              onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise),
              onDismiss: handleGigyaHide,
            }
          )
        } else {
          gigya.accounts.showScreenSet({
            screenSet: config.gigya.screenSets.registrationLogin,
            onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise),
            onHide: handleGigyaHide,
          })
        }

        return userPromise.promise.then(user => {
          $rootScope.$broadcast('auth', 'userLoggedIn')
          auth.sendLoggedInMessage(user)
        })
      }

      service.gigyaRegister = () => {
        const userPromise = $q.defer()

        const registrationParams = {
          screenSet: config.gigya.screenSets.registrationLogin,
          startScreen: 'gigya-register-screen',
        }

        if (config.gigya.sendRefererOnRegistration) {
          registrationParams.regSource = document.referrer
        }

        if (isMobileApp) {
          gigyaClient.showPlugin('accounts.screenSet', registrationParams, {
            onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise),
          })
        } else {
          registrationParams.onAfterSubmit = gigyaResponse =>
            handleGigyaSubmit(gigyaResponse, userPromise)
          gigya.accounts.showScreenSet(registrationParams)
        }

        return userPromise.promise.then(() => {
          $rootScope.$broadcast('registered')
        })
      }

      service.gigyaResetPassword = passwordResetToken => {
        const userPromise = $q.defer()

        if (isMobileApp) {
          gigyaClient.showPlugin(
            'accounts.screenSet',
            {
              screenSet: config.gigya.screenSets.registrationLogin,
              startScreen: 'gigya-reset-password-screen',
              passwordResetToken,
            },
            { onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise) }
          )
        } else {
          gigya.accounts.showScreenSet({
            screenSet: config.gigya.screenSets.registrationLogin,
            startScreen: 'gigya-reset-password-screen',
            onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise),
            passwordResetToken,
          })
        }

        return userPromise.promise
      }

      service.gigyaChangePassword = () => {
        const userPromise = $q.defer()

        if (isMobileApp) {
          gigyaClient.showPlugin(
            'accounts.screenSet',
            {
              screenSet: config.gigya.screenSets.profileUpdate,
              startScreen: 'gigya-change-password-screen',
            },
            { onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise) }
          )
        } else {
          gigya.accounts.showScreenSet({
            screenSet: config.gigya.screenSets.profileUpdate,
            startScreen: 'gigya-change-password-screen',
            onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise),
          })
        }

        return userPromise.promise
      }

      service.gigyaForgotPassword = () => {
        if (isMobileApp) {
          gigyaClient.showPlugin('accounts.screenSet', {
            screenSet: config.gigya.screenSets.registrationLogin,
            startScreen: 'gigya-forgot-password-screen',
          })
        } else {
          gigya.accounts.showScreenSet({
            screenSet: config.gigya.screenSets.registrationLogin,
            startScreen: 'gigya-forgot-password-screen',
          })
        }
      }

      service.gigyaProfile = () => {
        const userPromise = $q.defer()

        if (isMobileApp) {
          gigyaClient.showPlugin(
            'accounts.screenSet',
            {
              screenSet: config.gigya.screenSets.profileUpdate,
              startScreen: 'gigya-update-profile-screen',
              containerID: 'unata-gigya-profile-edit',
            },
            {
              onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise),
              onDismiss: () => $state.go('main.home'),
            }
          )
        } else {
          gigya.accounts.showScreenSet({
            screenSet: config.gigya.screenSets.profileUpdate,
            startScreen: 'gigya-update-profile-screen',
            onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise),
            containerID: 'unata-gigya-profile-edit',
          })
        }

        return userPromise.promise
      }

      service.gigyaCheckoutAddress = () => {
        const userPromise = $q.defer()

        if (isMobileApp) {
          gigyaClient.showPlugin(
            'accounts.screenSet',
            {
              screenSet: config.gigya.screenSets.profileUpdate,
              startScreen: 'update-delivery-address',
            },
            { onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise) }
          )
        } else {
          gigya.accounts.showScreenSet({
            screenSet: config.gigya.screenSets.profileUpdate,
            startScreen: 'update-delivery-address',
            onAfterSubmit: gigyaResponse => handleGigyaSubmit(gigyaResponse, userPromise),
          })
        }

        return userPromise.promise
      }

      service.gigyaInit = (view, passwordResetToken) => {
        // Checks the type of view requested (login/register/forgot/change) and executes the appropriate gigya.accounts method
        return service.sdkLoaded.then(() => {
          switch (view) {
            case 'login':
              return service.gigyaLogin()
            case 'register':
              return service.gigyaRegister()
            case 'reset':
              return service.gigyaResetPassword(passwordResetToken)
            case 'change':
              return service.gigyaChangePassword()
            case 'forgot':
              return service.gigyaForgotPassword()
            case 'profile':
              return service.gigyaProfile()
            case AUTH_ACTIONS.LOGOUT:
              return (config.gigya.syncLogout || isMobileApp) && service.logout()
          }
        })
      }

      // For consistency across differen auth services.
      service.init = service.gigyaInit

      /**
       * Returns promise that resolves into user gigya login information
       *
       * @returns { Promise }
       */
      service.getAccountInfo = () => {
        const defer = $q.defer()
        const handleAccountInfoResponse = response => {
          const { status, statusReason, UID, UIDSignature, signatureTimestamp } = response
          if (status === 'OK' || statusReason === 'OK') {
            const gigyaUser = {
              uid: UID,
              uid_signature: UIDSignature,
              signature_timestamp: signatureTimestamp,
            }

            defer.resolve(gigyaUser)
          } else {
            defer.reject(status)
          }
        }
        const accountInfoPayload = {
          callback: handleAccountInfoResponse,
        }

        if (isMobileApp) {
          gigyaClient
            .sendRequest('accounts.getAccountInfo')
            .then(({ data }) => handleAccountInfoResponse(data))
            .catch(({ data }) => defer.reject(data))
        } else {
          service.sdkLoaded.then(() => {
            gigya.accounts.getAccountInfo(accountInfoPayload)
          })
        }

        return defer.promise
      }

      /**
       * Syncs the platform session with Gigya by automatically logging the user in
       */
      service.syncLogin = () => {
        return service.getAccountInfo().then(service.authenticateGigyaUser)
      }

      /**
       * Authenticates Gigya users on the platform
       * @param { Object } gigyaUser - The Gigya user object
       *
       * @returns { Promise }
       */
      service.authenticateGigyaUser = ({ uid, uid_signature, signature_timestamp }) => {
        const loginPayload = {
          identifier_polytype: 'gigya',
          identifier_data: {
            uid,
            uid_signature,
            signature_timestamp,
            action: 'login',
          },
        }

        const handleAuthSuccess = () => {
          return authService.promiseActions.getUser().then(user => {
            // If logged in user does not have context intent then either use the non-logged in intent or the default.
            if (_.isNil(user.context.intent)) {
              auth.setContext({ intent: config.defaultShoppingContext })
            } else {
              // If user.context.intent was nil, auth.setContext eventually calls auth.updateUser
              auth.updateUser(user)
            }

            // Update the user's anonymousId on the backend as they are now logged in.
            anonymousIdService.updateBackendId()
          })
        }

        const handleAuthError = loginResponse => {
          return errorModalService.open(
            loginResponse.message || config.lang.defaultErrorMessage,
            'Back To Home'
          )
        }

        return authService.promiseActions
          .loginExternal(loginPayload)
          .then(
            handleAuthSuccess,
            () => {} // Do nothing. This is to prevent two modals from showing.
          )
          .catch(handleAuthError)
          .finally(() => {
            if ($uibModalStack.getTop()) {
              $uibModalStack.dismissAll()
            }
          })
      }

      /**
       * Destroys Gigya session
       *
       * @returns { Promise }
       */
      service.logout = () => {
        const defer = $q.defer()
        const handleAccountInfoSuccess = response => {
          response.status === 'OK' || response.statusReason === 'OK'
            ? defer.resolve(response)
            : defer.reject(response)
        }

        service.getAccountInfo().then(({ uid }) => {
          const logoutPayload = {
            UID: uid,
            callback: handleAccountInfoSuccess,
          }

          if (isMobileApp) {
            gigyaClient
              .sendRequest('accounts.logout')
              .then(({ data }) => handleAccountInfoSuccess(data))
              .catch(({ data }) => defer.reject(data))
          } else {
            $window.gigya.accounts.logout(logoutPayload)
          }
        })

        return defer.promise
      }

      document.addEventListener('deviceready', function () {
        if (isMobileApp && config.gigya && config.gigya.enabled && window.gigyaClient) {
          $window.gigya = gigyaClient
          gigyaClient.init(config.gigya.APIKey, 'us1.gigya.com')
          gigyaClient.addEventListener(gigyaClient.Event.LOGIN, this.gigyaLogin)
          sdkLoadedDefer.resolve()
        }
      })

      return service
    }
  )
