import angular from 'angular'
import template from './promoCollectionCell.html'

export default angular
  .module('PromoCollectionCellComponentModule', [])
  .directive('promoCollectionCell', $state => {
    return {
      scope: {
        promoCollection: '=',
        promotedMedia: '=',
      },
      replace: true,
      template,
      controller($scope, collectionModal, config, lang, mobileService) {
        $scope.lang = lang
        $scope.config = config
        $scope.mobile = mobileService
        $scope.openCollection = () => {
          if ($scope.promoCollection.count > config.maxNumberOfCollectionItemsPerModal) {
            $state.go('main.shop.collection', {
              collection_id: $scope.promoCollection.collection.id,
            })
          } else {
            collectionModal.open(`collections/${$scope.promoCollection.collection.id}`)
          }
        }
      },
    }
  })
