import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('flyerCategoriesAndPdfsServiceComponentModule', [])
  .service(
    'flyerCategoriesAndPdfsService',
    function flyerCategoriesAndPdfsService($q, $rootScope, $state, Restangular, tagsService) {
      const service = this

      service.clearFlyerCategories = () => {
        delete service.flyerCategories
      }
      service.clearPdfHrefs = () => {
        delete service.pdfHrefs
      }
      // Clear our cached flyer categories or PDF hrefs when the user's state is changed
      $rootScope.$on('auth', (event, action) => {
        if (action === 'userUpdated') {
          // Flyer categories, like flyer content, are highly dynamic and change according to various user state changes including:
          //  shopping context, store (store segments), profile data (customer segments), loyalty status

          // Flyer PDFs can change depending on the user's:
          //  store (store segments), loyalty status

          // Although ideally they would only be cleared from this service when those specific parts of the  user state change,
          //  that's currently not feasible, so we instead clear them both on any user state change

          // Flyer categories also need to be cleared when a user toggles betweeen regular/preview flyer
          service.clearFlyerCategories()
          // Flyer PDF Hrefs do not need to be cleared when a user toggles between regular/preview flyer because we need to
          //  fetch all of them at once anyway (in order to show links for each flyer in the flyer preview modal)
          service.clearPdfHrefs()
        }
      })

      // Fetch flyer categories
      // (If `params.preview_token` is provided, the categories will be specific to that preview flyer)

      service.fetchFlyerCategories = (params = {}, refresh) => {
        if (!refresh && service.flyerCategories) {
          return $q.resolve(service.flyerCategories)
        }
        return Restangular.all('flyer')
          .all('categories')
          .getList(params)
          .then(
            categories =>
              (service.flyerCategories = _.map(categories, category => {
                const categoryParams = { category_id: category.id }
                if (params.preview_timestamp) {
                  categoryParams.preview_timestamp = params.preview_timestamp
                }
                return {
                  name: category.name,
                  state: 'main.shop.flyer.category',
                  sort: true,
                  tags: tagsService.tags(),
                  id: category.id,
                  params: categoryParams,

                  // Add 'See All Deals' button to header in these views
                  pageHeaderButton: {
                    label: `See All Deals In ${category.name}`,
                    onclick: () => {
                      $state.go('main.shop.category', {
                        category_id: category.id,
                        tags: 'on_sale',
                        page: null,
                      })
                    },
                  },
                }
              }))
          )
      }

      const fetchFlyerPdfHref = (params = {}) =>
        Restangular.all('placements/flyer_pdf')
          .getList(params)
          .then(content =>
            // Return '' if there is no PDF href for this flyer
            content.length ? content[0].target.url : ''
          )
          // Return '' if the call fails
          .catch(() => '')

      // Fetch the PDF hrefs for the current and preview flyers

      service.fetchFlyerPdfHrefs = (previewFlyerData, previewTimestamp, refresh) => {
        if (!refresh && service.pdfHrefs) {
          return $q.resolve(service.pdfHrefs)
        }
        const pdfHrefPromises = _.reduce(
          previewFlyerData,
          (_pdfHrefPromises, currentFlyerOrPreviewFlyers, flyerType) => {
            // `previewFlyerData` has the following form: `{ current: { <current flyer data> }, previews: [{ <preview flyer data> }, ...] }``
            if (flyerType === 'current') {
              const flyerPdfCallParams = previewTimestamp
                ? { preview_timestamp: previewTimestamp }
                : {}
              _pdfHrefPromises.current = fetchFlyerPdfHref(flyerPdfCallParams)
            } else if (flyerType === 'previews' && currentFlyerOrPreviewFlyers) {
              _pdfHrefPromises.previews = $q.all(
                _.map(currentFlyerOrPreviewFlyers, previewFlyer =>
                  fetchFlyerPdfHref({ preview_token: previewFlyer.preview_token })
                )
              )
            }
            return _pdfHrefPromises
          },
          {}
        )

        return $q.all(pdfHrefPromises).then(pdfHrefs => (service.pdfHrefs = pdfHrefs))
      }
    }
  )
