import { createAction } from 'typesafe-actions'
import { FulfillmentType } from '@app/types/config'
import { ShoppingMode, ShoppingContextState } from './types'
import * as Constants from './constants'

/*
 * @deprecated use setShoppingContext instead
 */
export const setFulfillmentType = createAction(
  Constants.SET_FULFILLMENT_TYPE,
  (fulfillmentType: FulfillmentType) => fulfillmentType
)()

/*
 * @deprecated use setShoppingContext instead
 */
export const setShoppingMode = createAction(
  Constants.SET_SHOPPING_MODE,
  (mode: ShoppingMode) => mode
)()

export const setShoppingContext = createAction(
  Constants.SET_SHOPPING_CONTEXT,
  (modeAndContext: ShoppingContextState) => modeAndContext
)()
