import angular from 'angular'

export default angular
  .module('FileUploaderComponentModule', [])
  .component('fileUploader', {
    bindings: {
      uploaded: '=',
      price: '<?',
    },
    controller(lang, mobileService) {
      this.lang = lang
      this.mobileService = mobileService

      this.removeUpload = () => {
        this.uploaded = {}
      }
    },
    template: `
      <div class="file-uploader-wrapper">

        <div class="result-wrapper" ng-if="$ctrl.uploaded.success && !$ctrl.mobileService.isMobile">
          <div class="upload-success-wrapper">
            <div ng-init="$ctrl.showDeleteConfirm = false" ng-class="{shown: $ctrl.showDeleteConfirm}" class="delete-confirm">
              <div class="confirm-remove">
                {{::$ctrl.lang.values.imageUpload.wantToRemove}}
              </div>
              <div>
                <button ng-click="$ctrl.showDeleteConfirm = false;" class="button smaller dark" analytics-click="['configurable product image upload delete confirmation cancel button', 'click']">{{::$ctrl.lang.values.cancel}}</button>
                <button ng-click="$ctrl.showDeleteConfirm = false; $ctrl.removeUpload()" class="button smaller highlight" analytics-click="['configurable product image upload delete confirmation remove button', 'click']">{{::$ctrl.lang.values.remove}}</button>
              </div>
            </div>

            <img ng-src="{{$ctrl.uploaded.src}}" class="uploaded-pic" ng-if="$ctrl.uploaded.type !== 'application/pdf'" alt="{{::lang.aria.uploaded}}"/>
            <i class="icon-pdf" ng-if="$ctrl.uploaded.type === 'application/pdf'"></i>
            <div class="uploaded-info">
              <span>{{$ctrl.uploaded.name}}</span>
              <span role="button" ng-click="$ctrl.showDeleteConfirm = true" class="remove-button" ng-bind="::$ctrl.lang.values.remove"></span>
            </div>
          </div>
          <div class="success-message">
            <div class="message-icon"><i class="icon-checkmark-circle"></i></div>
            <div class="message-text">
              <span ng-bind="::$ctrl.lang.values.imageUpload.success"></span>
              <span ng-bind="::$ctrl.lang.values.imageUpload.uploadCTA.dragAndDropAfterSuccess"></span>
              <label>
                <input type='file' file-reader='$ctrl.uploaded' class='click-only'>
                <span ng-bind="::$ctrl.lang.values.imageUpload.uploadCTA.browse"></span>
              </label>
              <span ng-bind="::$ctrl.lang.values.imageUpload.uploadCTA.uploadADifferentImage"></span>
            </div>
          </div>
        </div>
        <div class="result-wrapper" ng-if="$ctrl.uploaded.success && $ctrl.mobileService.isMobile">
          <div class="upload-success-wrapper">
            <div class="upload-success-main-mobile-content">
              <div ng-init="$ctrl.showDeleteConfirm = false" ng-class="{shown: $ctrl.showDeleteConfirm}" class="delete-confirm">
                <div class="confirm-remove">
                  {{::$ctrl.lang.values.imageUpload.wantToRemove}}
                </div>
                <div>
                  <button ng-click="$ctrl.showDeleteConfirm = false;" class="button smaller dark" analytics-click="['configurable product image upload delete confirmation cancel button', 'click']">{{::$ctrl.lang.values.cancel}}</button>
                  <button ng-click="$ctrl.showDeleteConfirm = false; $ctrl.removeUpload()" class="button smaller highlight" analytics-click="['configurable product image upload delete confirmation remove button', 'click']">{{::$ctrl.lang.values.remove}}</button>
                </div>
              </div>
              <div role="button" ng-click="$ctrl.showDeleteConfirm = true" class="button smaller round darker icon-delete"></div>
              <img ng-src="{{$ctrl.uploaded.src}}" class="uploaded-pic" ng-if="$ctrl.uploaded.type !== 'application/pdf'" alt="{{::lang.aria.uploaded}}"/>
              <i class="icon-pdf" ng-if="$ctrl.uploaded.type === 'application/pdf'"></i>
              <div class="uploaded-info">
                <span>{{$ctrl.uploaded.name}}</span>
              </div>
            </div>
            <div class="change-image-box">
              <label>
                <input type='file' file-reader='$ctrl.uploaded' class='click-only'>
                <span>{{$ctrl.lang.values.changeImage}}</span>
              </label>
            </div>
          </div>
          <div class="upload-restrictions" ng-bind-html="::$ctrl.lang.values.imageUpload.uploadRestrictions"></div>
        </div>
        <div class="upload-wrapper" ng-if="!$ctrl.mobileService.isMobile && !$ctrl.uploaded.success">
          <div class="upload-wrapper-main-content">
            <i class="icon-image-upload upload-icon"></i>
            <div class="upload-text-container">
              <div class="upload-message">
                <span ng-bind="::$ctrl.lang.values.imageUpload.uploadCTA.dragAndDrop"></span>
                <label>
                  <input type='file' file-reader='$ctrl.uploaded' class='click-only'>
                  <span ng-bind="::$ctrl.lang.values.imageUpload.uploadCTA.browse"></span>
                </label>
              </div>
              <div class="upload-restrictions" ng-bind-html="::$ctrl.lang.values.imageUpload.uploadRestrictions"></div>
            </div>
          </div>
          <div class="upload-wrapper-error-content">
            <div ng-if="$ctrl.uploaded.invalidLang" class="error-message">
              <div class="message-icon"><i class="icon-error"></i></div>
              <div class="message-text"><span ng-bind="$ctrl.lang.values.imageUpload.invalid[$ctrl.uploaded.invalidLang]"></span></div>
            </div>
          </div>
        </div>
        <div class="upload-wrapper" ng-if="$ctrl.mobileService.isMobile && !$ctrl.uploaded.success">
          <label class="button dark darker-text hollow mobile-uploader">
            <input type='file' file-reader='$ctrl.uploaded' class='click-only'>
            <span class="upload-a-photo-icon"></span>
            <span class="upload-a-photo-text" ng-bind="::$ctrl.lang.values.imageUpload.uploadCTA.uploadPhoto"></span>
          </label>
          <div class="upload-restrictions" ng-bind-html="::$ctrl.lang.values.imageUpload.uploadRestrictions"></div>
          <div class="upload-wrapper-error-content">
            <div ng-if="$ctrl.uploaded.invalidLang" class="error-message">
              <div class="message-icon"><i class="icon-error"></i></div>
              <div class="message-text"><span ng-bind="$ctrl.lang.values.imageUpload.invalid[$ctrl.uploaded.invalidLang]"></span></div>
            </div>
          </div>
        </div>

        <input type="file" file-reader="$ctrl.uploaded" class="drag-drop-only" drag-drop-only="true">
        <div class="file-uploader-price" ng-if="$ctrl.price">+ {{::$ctrl.price | currency}}</div>
      </div>
    `,
  })
  .directive('fileReader', function ($http, config) {
    return {
      scope: {
        fileReader: '=',
        dragDropOnly: '=',
      },
      link: (scope, elem) => {
        let uploadedObjectData = {}

        const dragOverOrEnter = dragEvent => {
          const dragParentElement = angular.element(dragEvent.target.parentNode)
          dragParentElement.addClass('dragging')
        }

        const dragLeaveOrEnd = dragEvent => {
          const dragParentElement = angular.element(dragEvent.target.parentNode)
          dragParentElement.removeClass('dragging')
        }

        const isFileTypeValid = type => {
          return config.imageUploadRestrictions.types.indexOf(type) > -1
        }

        const isFileSizeValid = size => {
          return config.imageUploadRestrictions.size > size
        }

        const upload = file => {
          const formData = new FormData()

          formData.append('file', file, file.name)
          formData.append('polytype', 'configurable_product')

          // We need to manually set `Content-Type: undefined` here since we are using FormData,
          // the $http.post service will automatically insert a boundary header when `Content-Type: undefined`
          // also, we are using $http because Restangular forces an app reload if the API throws an exception, which can happen depending on file type

          return $http.post('/api/v2/upload', formData, {
            headers: {
              'Content-Type': undefined,
            },
          })
        }

        elem.on('change', changeEvent => {
          const reader = new FileReader()
          const file = changeEvent.target.files[0]

          uploadedObjectData = {}

          reader.onload = loadEvent => {
            scope.$apply(() => {
              if (isFileTypeValid(file.type) && isFileSizeValid(file.size)) {
                upload(file).then(
                  uploadedResp => {
                    uploadedObjectData = {
                      success: true,
                      invalid: false,
                      src: loadEvent.target.result,
                      url: uploadedResp.data.url,
                      name: uploadedResp.data.name,
                      id: uploadedResp.data.id,
                    }

                    scope.fileReader = uploadedObjectData
                  },
                  () => {
                    // Handle any API after-post errors with generic error message
                    uploadedObjectData = {
                      success: false,
                      invalidLang: 'generic',
                    }

                    scope.fileReader = uploadedObjectData
                  }
                )
              } else {
                if (!isFileTypeValid(file.type)) {
                  uploadedObjectData = {
                    success: false,
                    invalidLang: 'type',
                  }
                } else if (!isFileSizeValid(file.size)) {
                  uploadedObjectData = {
                    success: false,
                    invalidLang: 'size',
                  }
                }

                scope.fileReader = uploadedObjectData
              }
            })
          }

          reader.readAsDataURL(file)
        })

        if (scope.dragDropOnly) {
          elem.on('click', clickEvent => {
            clickEvent.preventDefault()
          })

          elem.on('dragover dragenter', dragOverOrEnter)
          elem.on('dragleave dragend drop', dragLeaveOrEnd)
        }
      },
    }
  })
