import angular from 'angular'
import template from './simplifiedHeader.html'

class SimplifiedController {
  constructor(lang) {
    this.lang = lang
    this.customUiSref = this.customUiSref || 'main.home'
  }
}

export default angular.module('SimplifiedHeaderModule', []).component('simplifiedHeader', {
  template,
  bindings: {
    screenReaderTitle: '<?',
    customUiSref: '=?',
  },
  controller: SimplifiedController,
})
