/*
 * For a list of payment providers and their compatibility with the new checkout redesign, please see:
 * https://instacart.atlassian.net/wiki/spaces/ENG/pages/995098979/Payment+Providers+and+new+checkout+styling
 *
 * For each payment provider service, you'll want to set the css class it should use with the new checkout (paymentForm.styl):
 *    paymentGatewayProvider.registerGateway({
 *      name: ...
 *      ...
 *      ...
 *      newCheckoutCssClass: 'provider-generic_styling'   // This is useful for most, but use a custom style if need be!
 *    });
 */
import angular from 'angular'

import { CART_TYPES } from '@app/angular/constants/cart'
import {
  CHECKOUT_STEP_IDS,
  FULFILLMENT_TYPES,
  RESET_CHECKOUT_STEPS,
} from '@app/angular/constants/checkout'
import { PAYMENT_TYPES, PAYMENT_CONTEXTS } from '@app/angular/constants/payments'
import { USER_PAYMENT_ACTION } from '@app/angular/services/modules/analytics/analyticsConstants'

import template from './paymentForm.html'

const PAYMENT_FORM_ID = 'payment'
const EVENT_TRIGGER_DELAY = 250
const NOTHING_TO_SHOW_GATEWAY = true
const BRAIN_TREE_NAME = 'Braintree'
const CREDIT_CARD_TYPE = 'auth_capture'

class PaymentForm {
  constructor(
    $scope,
    $element,
    $timeout,
    $uibModal,
    auth,
    cartService,
    checkoutService,
    config,
    checkoutAnalytics,
    giftCardModalService,
    lang,
    mobileService,
    paymentGateway,
    reservationService,
    utilService
  ) {
    const { payment_types } = auth.user.store
    const { loadStaticImage } = utilService

    Object.assign(this, {
      $uibModal,
      config,
      hasErrors: false,
      hasBeenEdited: false,
      lang,
      loadStaticImage,
      NOTHING_TO_SHOW_GATEWAY,
      payment_types,
      isBillingAddressSameAsDelivery: true,
      gatewayInformation: {},
    })

    this.services = {
      $scope,
      $element,
      $timeout,
      auth,
      cartService,
      checkout: checkoutService,
      checkoutAnalytics,
      giftCardModalService,
      mobile: mobileService,
      paymentGateway,
      reservationService,
    }

    // Assigning to `$scope` allows the payment providers to access required resources
    Object.assign($scope, {
      lang,
      payment: null,
      paymentContext: PAYMENT_CONTEXTS.CHECKOUT,
      checkoutNewCreditCard: success => {
        $timeout(() => {
          const { payment } = this

          checkoutAnalytics.addPaymentOption({
            payment,
            success,
            user_action: USER_PAYMENT_ACTION.PAYMENT_CHANGE,
          })
        }, EVENT_TRIGGER_DELAY)
      },
      onShowCardForm: () => checkoutAnalytics.addCreditCardOpen(),
      doSave: () => this.doSave(),
      toggleIsShowingAddCard: isNewCard => (this.isNewCard = isNewCard),
    })
  }

  /**
   * Validates current paymentType
   * @param {string} paymentType
   *
   * @returns {boolean}
   */
  isSelectedPaymentValid(paymentType) {
    if (!paymentType) return false

    switch (paymentType) {
      case PAYMENT_TYPES.HOUSE_ACCOUNT:
        return this._isHouseAccountValid(this.payment?.house_account?.id)
      default:
        return true
    }
  }

  /**
   * Validates a given house account object
   *
   * @param {object} houseAccountNumber - Object that contains an *id* key
   * @returns {boolean}
   */
  _isHouseAccountValid(houseAccountNumber) {
    const houseAccountLength = houseAccountNumber?.length || 0
    const { maxHouseAccountLength, shouldValidateHouseAccount } = this.config

    if (!shouldValidateHouseAccount) {
      return houseAccountLength !== 0
    }

    return houseAccountLength === maxHouseAccountLength
  }

  /**
   * Initialise tasks required for payment after constructor
   * @returns { undefined }
   */
  $onInit() {
    const { $scope, reservationService } = this.services

    // Respond to payment object changes and reservation (timeslot) changes
    const watchPaymentGroup = [() => this.payment, () => reservationService.reservation]

    $scope.$watchGroup(watchPaymentGroup, () => {
      this._updatePaymentOptions()
      this._hasErrors()
    })

    // If error, show an error message
    $scope.$watch(
      () => $scope.error,
      () => {
        const message = $scope.error
        if (message) {
          this.onError({ error: { message } })
        }
      }
    )

    // Indicate if this section has ever been expanded for editing
    $scope.$watch(
      () => this.editMode,
      () => {
        this.hasBeenEdited = this.hasBeenEdited || this.editMode
        $scope.lastEditMode = this.editMode

        // Abort edit if this section is set to read only on modify mode
        if (this.isModifyAndPaymentReadOnly()) {
          this.abortEdit()
        }
      }
    )

    // When all the sections collapse, show if there are errors
    $scope.$on(RESET_CHECKOUT_STEPS, () => this._hasErrors())
  }

  /**
   * Returns the current fullfilment type
   * @returns { string }
   */
  get fulfillmentType() {
    return this.services.auth.getContext().intent
  }

  /**
   * Returns whether or not payment type has auth capture (used for auth notice)
   * @returns { string }
   */
  get doesPaymentTypeHaveAuth() {
    return this.payment?.payment_type === PAYMENT_TYPES.AUTH_CAPTURE
  }

  /**
   * Returns the current payment type
   * @returns { string }
   */
  get currentPaymentType() {
    const { lang, payment } = this
    return this.getPaymentTypeString(payment?.payment_type) || lang.values.pleaseSelectPaymentMethod
  }

  isSingleOption() {
    const {
      payment_types,
      fulfillmentType,
      services: { paymentGateway },
    } = this
    const paymentMethods = payment_types && payment_types[fulfillmentType]

    if (paymentMethods && paymentMethods.length === 1) {
      const gatewayInformation = paymentGateway.get(paymentMethods[0])
      return gatewayInformation.noSettingsRequired
    }

    return false
  }

  isModifyAndPaymentReadOnly() {
    const {
      config,
      services: { cartService },
    } = this

    return cartService.cart.order && config.modifyOrder.readOnlyPayment
  }

  /**
   * Returns a human readable form of the payment type passed in
   * @param   paymentType <string>      The payment type (credit, debit, etc)
   * @returns { string }
   */
  getPaymentTypeString(paymentType) {
    const { fulfillmentType, lang } = this
    const paymentTypeStrings = paymentType && lang.values.paymentTypes[paymentType]
    return paymentTypeStrings ? paymentTypeStrings[fulfillmentType] : null
  }

  /**
   * Changes the section to edit mode by calling the edit prop function
   *
   * @returns { undefined }
   */
  enterEditMode() {
    this.hasErrors = false
    this.edit({ step: PAYMENT_FORM_ID })
    this.services.checkoutAnalytics.viewPaymentOptions({ payment: {} })

    // if editing credit-card-list
    // focus on the radial button with the selected credit card
    this.services.$timeout(() => {
      // eslint-disable-next-line no-unused-expressions
      this.services.$element[0]?.querySelector('.credit-card-list-selected-card')?.focus()
    })
  }

  /**
   * Updates the payment type, setting the payment gateway to the required provider
   * @param   { string } newPayType            Optional: The new payment type (credit, debit, etc)
   * @param   { boolean }  logAnalytics:     Optional: True if logging the edit_payment event
   * @returns { undefined }
   */
  updatePaymentType(newPayType = null, logAnalytics = false) {
    const { payment, editMode, fulfillmentType, payment_types } = this
    const { $scope, checkoutAnalytics, paymentGateway } = this.services
    let paymentTypeValid
    let selectedGateway
    let gatewayInformation
    let paymentCssClassName

    $scope.toggleIsShowingAddCard(false)

    if (payment && (newPayType || newPayType === '')) {
      payment.payment_type = newPayType
    }

    // If there's only one payment option and it's not selected - then select it!
    if (payment && !payment.payment_type) {
      const paymentIntentTypes = this.getPaymentIntentTypes()
      payment.payment_type = paymentIntentTypes.length === 1 ? paymentIntentTypes[0] : null
    }

    const { payment_type } = payment || {}
    if (payment_type) {
      if (logAnalytics) {
        checkoutAnalytics.editPaymentOptions({
          payment,
          paymentLabel: this.getPaymentTypeString(payment_type),
          success: true,
          user_action: USER_PAYMENT_ACTION.PAYMENT_CHANGE,
        })
      }
      const gatewayService = paymentGateway.getService(payment_type)
      const gatewayContent = paymentGateway.getTemplate(payment_type)
      selectedGateway = gatewayContent || NOTHING_TO_SHOW_GATEWAY
      gatewayInformation = paymentGateway.get(payment_type)
      this.gatewayInformation = gatewayInformation
      paymentTypeValid = gatewayService.status // gatewayService.status is an object -> {ready: T/F} so that we can get that inheritance magic
      paymentCssClassName = gatewayInformation.newCheckoutCssClass

      const paymentMethods = payment_types && payment_types[fulfillmentType]
      if (
        gatewayInformation.noSettingsRequired &&
        editMode &&
        paymentMethods &&
        paymentMethods.length === 1
      ) {
        this.doSave()
      }
    }

    Object.assign(this, {
      paymentTypeValid,
      paymentCssClassName,
      selectedGateway,
      gatewayInformation,
    })
  }

  getPaymentIntentTypes() {
    const { payment_types } = this
    const { auth, mobile } = this.services
    const isCatering = this.services.cartService.getCurrentCartType() === CART_TYPES.CATERING
    const intent = !isCatering ? auth.intent.toString() : FULFILLMENT_TYPES.CATERING
    const paymentIntentTypes = payment_types[intent]

    if (!paymentIntentTypes?.length) {
      return isCatering ? [PAYMENT_TYPES.CASH] : []
    }
    if (mobile.isMobileApp() || window.inAppBrowser) {
      return paymentIntentTypes.filter(p => p !== 'paypal')
    }
    return paymentIntentTypes
  }

  /**
   * Opens the gift card modal
   *
   * @returns { undefined }
   */
  showAddGiftCard() {
    const { payment } = this
    const { giftCardModalService } = this.services
    giftCardModalService.open({ payment })
  }

  /**
   * Removes a gift card by index and saves the change to the server
   *
   * @returns { undefined }
   */
  removeGiftCard(index) {
    const { payment } = this
    if (payment.gift_cards) {
      payment.gift_cards.splice(index, 1)
      payment.put()
    }
  }

  /**
   * Updates the user billing information
   *
   * @returns {Promise}
   */
  updateBillingInformation() {
    const { auth } = this.services
    const { billing_address } = auth.user
    return auth.updateProfile({ billing_address })
  }

  /**
   * Triggered by the save button
   * Validates that payment is ready to save and triggers the save method if so
   *
   * @returns { undefined }
   */
  doSave() {
    const { payment, paymentTypeValid } = this
    const { $timeout, auth, checkout } = this.services
    const { ready: isValid } = paymentTypeValid || {}

    if (!this.isSelectedPaymentValid(payment?.payment_type)) {
      return
    }

    if (this.suppressInitialAutoClose) {
      $timeout(() => (this.suppressInitialAutoClose = false), 500)
      return
    }

    if (this.config.enableBillingInformation && payment?.payment_type === CREDIT_CARD_TYPE) {
      if (!checkout.isBillingAddressValid(auth.user?.billing_address)) {
        this.hasErrors = this.editMode
        return
      }

      this.updateBillingInformation().then(() => this.onSave({ isValid }))
    } else {
      const { hasBeenEdited } = this
      this.onSave({ isValid, hasBeenEdited })
    }
  }

  /**
   * Returns is we should show the save button outside the payment provider component.
   * In the future, all payment providers should implement a customizable select/save button
   * that executes a given callback fn when clicked. Right now, only Stripe is configured
   * to be used like this.
   *
   * @returns { boolean }
   */
  shouldShowSaveButton() {
    if (this.gatewayInformation && this.gatewayInformation.hideSaveButton) {
      return false
    }

    return this.editMode
  }

  /**
   * Returns if we should show the gift cards outside the payment provider.
   * In the future, gift cards should be part of every payment provider. Right now, only
   * Stripe is configured to use gift cards internally
   *
   * @returns { boolean}
   */
  shouldShowGiftCardsLink() {
    if (this.gatewayInformation && this.gatewayInformation.hideGiftCardLink) {
      return false
    }

    return this.config.showGiftCards && this.editMode && !this.isNewCard
  }

  shouldShowPaymentWarning() {
    return Boolean(this.lang.values?.paymentWarning && this.lang.values?.paymentWarning !== '')
  }

  /**
   * [Private] Updates the payment options presented in the UI and invalidates the current method if need be
   */
  _updatePaymentOptions() {
    const { fulfillmentType, payment } = this
    const { $scope, auth } = this.services
    const { payment_types } = auth.user?.store || {}

    const paymentOptions = payment_types[fulfillmentType]
    const isValidPaymentType = paymentOptions?.includes(payment?.payment_type)

    $scope.payment = payment // This allows the payment providers to access payment
    this.payment_types = payment_types
    this.updatePaymentType(!isValidPaymentType ? '' : null)
  }

  /**
   * [Private] Checks for errors and sets "hasErrors" accordingly
   */
  _hasErrors() {
    const { $timeout, checkout } = this.services

    // Delay to allow time for the step to edit
    $timeout(() => {
      const {
        editMode,
        fulfillmentType,
        gatewayInformation: { name: gatewayName } = {},
        hasBeenEdited,
        isNewCard,
        payment: { payment_type } = {},
        paymentTypeValid: { loaded, ready } = {},
        stepNames,
      } = this

      const isSelectedPaymentValid = this.isSelectedPaymentValid(payment_type)
      const isBrainTree = gatewayName === BRAIN_TREE_NAME
      const isInitialized = payment_type && fulfillmentType

      const invalidStatuses = stepNames
        .filter(step => step !== CHECKOUT_STEP_IDS.PAYMENT)
        .map(step => checkout.stepsStatuses[step])
        .filter(status => status && status.valid === false)

      const isAllOtherSectionsValid = invalidStatuses.length === 0

      this.hasErrors =
        loaded && // Payment provider has loaded
        !editMode && // Payment section is in read only mode
        isAllOtherSectionsValid && // All other sections are valid (otherwise payment section is N/A, not error)
        hasBeenEdited && // The payment section has been opened at least once
        isInitialized && // A payment type has been selected as well as a valid delivery mode
        // Not ready (Brain tree excluded), or is a new card, or selected payment type invalid
        ((!ready && !isBrainTree) || !!isNewCard || !isSelectedPaymentValid)
    })
  }
}

export default angular.module('PaymentFormModule', []).component('paymentForm', {
  bindings: {
    editMode: '<',
    payment: '<',
    onError: '&',
    onSave: '&',
    edit: '&',
    abortEdit: '&',
    hideChevron: '<',
    stepNames: '<',
    showDeferredPaymentNotice: '<',
    suppressInitialAutoClose: '<',
  },
  template,
  controller: PaymentForm,
})
