import { MEMBERSHIP_STATUSES } from '@app/angular/constants/express'
import angular from 'angular'
import template from './icExpressAd.html'

class IcExpressAd {
  constructor($filter, config, lang, mobileService, utilService) {
    Object.assign(this, {
      config,
      lang,
      mobileService,
      utilService,
      currencyFilter: $filter('currency'),
      parsedList: [],
    })
  }

  $onInit() {
    const { monthly, yearly } = this.config.icExpress.plans

    this.parsedList = this.list.map(item => {
      return {
        ...item,
        name: this.lang.template(item.name, {
          date: this.cancellationDate,
          amount: this.currencyFilter(
            this.config.icExpress.minimumOrderAmount,
            this.config.currencySymbol,
            0
          ),
          savingsAmount: this.currencyFilter(
            this.config.icExpress.averageSavingsAmount,
            this.config.currencySymbol,
            0
          ),
          yearlyPrice: this.currencyFilter(yearly.price, this.config.currencySymbol, 0),
          monthlyPrice: this.currencyFilter(monthly.price, this.config.currencySymbol),
        }),
        desc: this.lang.template(item.desc, {
          date: this.cancellationDate,
          amount: this.currencyFilter(
            this.config.icExpress.minimumOrderAmount,
            this.config.currencySymbol,
            0
          ),
          savingsAmount: this.currencyFilter(
            this.config.icExpress.averageSavingsAmount,
            this.config.currencySymbol,
            0
          ),
          yearlyPrice: this.currencyFilter(yearly.price, this.config.currencySymbol, 0),
          monthlyPrice: this.currencyFilter(monthly.price, this.config.currencySymbol),
        }),
      }
    })
  }

  /**
   * Takes the title that is passed and capitailzes it
   *
   * @returns { string }
   */
  get capitalizedTitle() {
    return this.title.toUpperCase()
  }

  showPlanSelector() {
    return (
      this.config.icExpress.enablePlanPurchaseSelection &&
      this.userSubscriptionStatus === MEMBERSHIP_STATUSES.MEMBERSHIP_ELIGIBLE &&
      this.eligibleSubscriptionPlans
    )
  }
}

export default angular.module('IcExpressAdComponentModule', []).component('icExpressAd', {
  bindings: {
    title: '@',
    subTitle: '@',
    ctaButtonLabel: '@',
    ctaLinkButtonShow: '<',
    ctaLinkButtonLabel: '@',
    ctaLinkButtonDescription: '@',
    list: '<',
    onCtaButtonClicked: '&',
    onCtaLinkButtonClicked: '&',
    enableRetailerTheme: '<',
    cancellationDate: '@',
    userSubscriptionStatus: '<',
    eligibleSubscriptionPlans: '<',
    onPlanSelectedCallback: '<',
    planIdToSubscribeTo: '<',
  },
  template,
  controller: IcExpressAd,
})
