import angular from 'angular'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import { getBannerSegmentPayload } from '@app/utils/banners'
import { parse } from '@app/utils/json'

export const BANNER_FETCH_EVENT = 'headerBanners:fetch'

export default angular
  .module('placementsServicesModule', [])
  .service(
    'placementsService',
    function placementsService(
      $location,
      $rootScope,
      Restangular,
      analytics,
      canvas,
      collectionModal,
      mobileService,
      utilService,
      videoModalService
    ) {
      const service = this

      service.logPromotedPlacement = (placement, location, position) => {
        canvas.logInteraction(placement)
        analytics.logEvent(`${location} placement`, 'click')

        const type = placement.route
        analytics.setActivePlacementImpressionToStore(type)

        if (!placement || !placement.id) return

        const segmentPayload = {
          ...analytics.getActivePlacementImpression(),
          ...getBannerSegmentPayload(),
          id: placement.id,
          placement_name: placement.name,
          content_type: placement.media.type,
          target_action: placement.target.type,
          position,
        }
        analytics.logSegmentEvent('store.click_promoted_placement', segmentPayload)
      }

      // Listen for events outside of Angular app
      eventEmitter.on(EVENT_TYPES.LOG_PROMOTED_PLACEMENT, ({ placement, location, position }) =>
        service.logPromotedPlacement(placement, location, position)
      )

      /**
       * Handles the behaviour of when a placement is clicked and where it should be navigated to
       *
       * @param {object} placement - Obj that contains placement target and information
       * @returns undefined
       */
      service.handleImagePlacementClick = (placement, location, position) => {
        service.logPromotedPlacement(placement, location, position)

        if (placement.target) {
          const {
            target: { url, openIn, product, offer, collection },
          } = placement

          if (url) {
            if (videoModalService.isVideoURL(url)) return videoModalService.open(url)
            if (utilService.isLinkInternal(url)) return utilService.openInternalUrl(url)
            if (openIn === 'new_tab') return window.open(url, '_blank')
            return (window.location = url)
          }

          if (product) return $location.path(`/product/${product.id}`)
          if (offer) return $location.path(`/offers/${offer.id}`)
          if (collection) return collectionModal.open(`collections/${collection.id}`)
        }
      }

      eventEmitter.on(EVENT_TYPES.LOG_IMAGE_PLACEMENT, ({ placement, location }) =>
        service.handleImagePlacementClick(placement, location)
      )

      service.getHeroContent = isMobile => {
        const endpoint = isMobile ? 'mobile_homepage_hero' : 'homepage_hero'
        return Restangular.all('placements')
          .all(endpoint)
          .getList()
          .catch(() => [])
      }

      service.getBannerContent = () =>
        Restangular.all('placements')
          .all('homepage_banners')
          .getList()
          .catch(() => [])

      service.getOrderConfirmationPlacement = isMobile =>
        Restangular.all('placements')
          .all(isMobile ? 'mobile_order_confirmation' : 'order_confirmation')
          .getList()
          .catch(() => [])

      service.getHomePageCollections = () =>
        Restangular.all('placements')
          .all('homepage_collections')
          .getList()
          .catch(() => [])

      // Function to fetch catalog banners. Will return a cached result past the
      // first request unless given a truthy value for 'refresh'
      service.getCatalogBanners = refresh => {
        if (!refresh && service.catalog_banners) {
          return service.catalog_banners
        }
        service.catalog_banners = Restangular.all('placements')
          .all('catalog_banners')
          .getList()
          .catch(() => [])
        return service.catalog_banners
      }

      // Function to fetch catalog collections. Will return a cached result past
      // the first request unless given a truthy value for 'refresh'
      service.getCatalogCollections = refresh => {
        if (!refresh && service.catalog_collections) {
          return service.catalog_collections
        }
        service.catalog_collections = Restangular.all('placements')
          .all('catalog_collections')
          .getList()
          .catch(() => [])
        return service.catalog_collections
      }

      service.getHeaderBanners = () =>
        Restangular.all('placements')
          .all(`${mobileService.isMobile ? 'mobile_' : ''}header_banner`)
          .getList()
          .then(list => $rootScope.$emit(BANNER_FETCH_EVENT, list) && list)
          .catch(() => [])

      service.getFeaturedDepartmentById = (featuredDepartmentId, queryStringCollectionList) =>
        Restangular.all('placements')
          .one('featured_department', featuredDepartmentId)
          .get()
          .then(featuredDepartment => {
            if (queryStringCollectionList === undefined) {
              return featuredDepartment.item
            }
            const fetchedFeaturedDepartment = featuredDepartment.item
            fetchedFeaturedDepartment.target.collection_list = queryStringCollectionList
            return fetchedFeaturedDepartment
          })
          .catch(() => [])

      // Function to fetch featured departments. Will return a cached result past
      // the first request unless given a truthy value for 'refresh'
      service.getFeaturedDepartments = refresh => {
        if (!refresh && service.featured_departments) {
          return service.featured_departments
        }
        service.featured_departments = Restangular.all('placements')
          .all('featured_department')
          .getList()
          .catch(() => [])
        return service.featured_departments
      }

      // Parsing Query String for CollectionID List Passed as Query Parameter Functionality
      service.getURLPassedCollectionsList = stateParams => {
        return Array.isArray(parse(`${stateParams.collections}`))
          ? JSON.parse(`${stateParams.collections}`)
          : []
      }
    }
  )
