import angular from 'angular'
import { MEMBERSHIP_STATUSES } from '@app/angular/constants/express'
import template from './icExpressProfile.html'

class IcExpressProfile {
  constructor(config, lang, utilService) {
    Object.assign(this, { config, lang, utilService })
  }

  get imageURL() {
    const { profile } = this.config.icExpress.placements

    return this.utilService.loadAssetImage(profile.image)
  }

  get text() {
    const { icExpress } = this.config
    const { trialText, membershipText } = icExpress.placements.profile

    return this.lang.template(
      this.membershipStatus === MEMBERSHIP_STATUSES.TRIAL ? trialText : membershipText,
      {
        user: this.username,
        name: icExpress.name,
      }
    )
  }
}

export default angular.module('IcExpressProfileModule', []).component('icExpressProfile', {
  template,
  bindings: {
    username: '@',
    useIcTheme: '<',
    membershipStatus: '<',
  },
  controller: IcExpressProfile,
  controllerAs: '$ctrl',
})
