import angular from 'angular'
import dayjs from 'dayjs'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { DEFAULT_TIMESLOT_DATE_FORMAT } from '@app/angular/constants/timeslots'
import { utils as timeslotUtils } from '@instacart/enterprise-timeslots'
import { hasBannerImage } from '@app/utils/banners.ts'

export default angular
  .module('ShoppingSelectorConfirmationControllerComponentModule', [])
  .controller(
    'ShoppingSelectorConfirmationController',
    function (
      $controller,
      $scope,
      $state,
      addressList,
      auth,
      cartService,
      config,
      eComLightService,
      errorModalService,
      modalOptions,
      shoppingContextService
    ) {
      $controller('ContextSelectorController', {
        $scope,
      })

      $scope.shoppingContextService = shoppingContextService
      $scope.hasBannerImage = hasBannerImage()

      // Switch to default store and go to context selector view.

      function exitB2bMode() {
        auth
          .update({ store_id: null })
          .then(() => {
            $scope.$parent.setShoppingSelectorStage('shoppingContext')
          })
          .catch(() => {
            errorModalService
              .open(config.lang.defaultErrorMessage, 'Reload', 'Back To Home')
              .then(() => {
                window.location.reload()
              })
              .catch(() => {
                $state.go('main.home', null, { reload: true })
              })
          })
      }

      // When user selects "Change" next to the store/zip code, proceed differently depending on whether or not they are in B2B mode

      $scope.changeStoreOrZipcode = function () {
        modalOptions.reset()

        if (auth.user.store.is_b2b) {
          exitB2bMode()
        } else {
          if (config.eComLight?.enabled && eComLightService.inEComLightMode) {
            shoppingContextService.setSelectedFulfillmentType(SHOPPING_MODES.CATERING)
          }
          $scope.$parent.setShoppingSelectorStage('storeSelector')
        }
      }

      $scope.cartEmpty = function () {
        // Cart doesn't exist, or is empty.
        return !$scope.cartService.cart || !$scope.cartService.cart.items.length
      }

      $scope.changeTimeslot = () => {
        modalOptions.reset()
        $scope.$parent.setShoppingSelectorStage('timeslot')
      }

      $scope.changeBanner = () => {
        modalOptions.reset()
        $scope.$parent.setShoppingSelectorStage('bannerSelector')
      }

      const format = 'hh:mm:ss'
      const time = dayjs()
      const morningStartTime = dayjs('04:00:00', format)
      const afternoonStartTime = dayjs('12:00:00', format)
      const eveningStartTime = dayjs('18:00:00', format)

      $scope.unifiedWelcomeMessage = function () {
        if (time.isBetween(morningStartTime, afternoonStartTime)) {
          return 'Good Morning'
        }
        if (time.isBetween(afternoonStartTime, eveningStartTime)) {
          return 'Good Afternoon'
        }
        return 'Good Evening'
      }

      // prevents b2b mode infinite loop when no timeslots are available
      $scope.startContinueShopping = function () {
        auth.user.store.is_b2b ? $scope.$parent.processModalCloseClick() : $scope.$dismiss()
      }

      // show change link if more than one selector modes are enabled
      $scope.showChangeLink =
        Object.values(config.shoppingSelector).filter(({ exists }) => exists).length > 1

      $scope.timeslotLabelFn = () =>
        timeslotUtils.getMainLabel($scope?.reservationService?.displayReservation, {
          verboseLevel: 1,
          returnLabel: false,
          format: $scope.reservationService.reservation
            ? config.apiDateFormat
            : DEFAULT_TIMESLOT_DATE_FORMAT,
        })

      const context = auth.getContext()
      if (context.intent === 'delivery') {
        $scope.deliveryAddress = auth.getAddressFromContext()
        if ($scope.deliveryAddress.id) {
          addressList
            .getAddresses()
            .then(
              () =>
                ($scope.deliveryAddress = addressList.getAddressFromId($scope.deliveryAddress.id))
            )
        }
      }

      if (config.multipleBanners.enabled && context.intent === 'delivery') {
        const bannerSlug = auth.user.store?.store_banner?.key
        const bannerInfo = config.multipleBanners?.banners?.find(b => b.bannerSlug === bannerSlug)
        $scope.bannerSlug = bannerInfo?.bannerSlug
        $scope.bannerName = bannerInfo?.name
      }
    }
  )
