import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('AddressValidationModalControllerModule', [])
  .controller(
    'AddressValidationModalController',
    function AddressValidationModalController(
      $rootScope,
      $state,
      $uibModalInstance,
      auth,
      compareCartService,
      errorModalService,
      isValid,
      lang,
      originalAddress,
      recommendedAddresses
    ) {
      const vm = this
      vm.lang = lang

      vm.isValid = isValid
      vm.recommendedAddresses = recommendedAddresses
      vm.originalAddress = _.mapValues(originalAddress, field => {
        return {
          value: field,
        }
      })

      // Init selectedAddress model for use in radio checkbox
      vm.selectedAddress = {
        address:
          vm.isValid && vm.recommendedAddresses.length > 0
            ? vm.recommendedAddresses[0]
            : vm.originalAddress,
      }

      vm.editAddress = () => {
        if (originalAddress) {
          $rootScope.$broadcast('editAddressItem', {
            itemId: originalAddress.id,
            initialAddress: originalAddress,
          })
        }
        $uibModalInstance.dismiss()
      }

      vm.confirmSelectedAddress = () => {
        // map selectedAddress vm object to usable payload for update user endpoint
        // if value is not present, set to undefined for JSON.stringify compare
        const selectedAddressPayload = {
          address1: vm.selectedAddress.address.address1
            ? vm.selectedAddress.address.address1.value
            : undefined,
          address2: vm.selectedAddress.address.address2
            ? vm.selectedAddress.address.address2.value
            : undefined,
          address3: vm.selectedAddress.address.address3
            ? vm.selectedAddress.address.address3.value
            : undefined,
          city: vm.selectedAddress.address.city ? vm.selectedAddress.address.city.value : undefined,
          postal_code: vm.selectedAddress.address.postal_code
            ? vm.selectedAddress.address.postal_code.value
            : undefined,
          province: vm.selectedAddress.address.province
            ? vm.selectedAddress.address.province.value
            : undefined,
        }

        const updatedAddress = {
          delivery_address: _.assign(angular.copy(originalAddress), selectedAddressPayload),
        }

        $uibModalInstance.close(updatedAddress)
      }
    }
  )
