import angular from 'angular'
import { wordpress } from '@instacart/enterprise-services'

export default angular
  .module('wordpressRestApiServicesModule', [])
  .service(
    'wordpressRestApiService',
    function wordpressRestApiService($q, $rootScope, Restangular, auth, config, mobileService) {
      const service = this

      let loggedInStatus = null

      service.getMobileMenuItem = menuSlug => {
        return wordpress.fetchWpMenu(menuSlug)
      }

      service.getCategories = () => {
        return Restangular.oneUrl(
          'main.search',
          `${config.dotComModule.url}/wp-json/wp/v2/categories`
        ).get()
      }

      service.getTags = () => {
        return Restangular.oneUrl(
          'main.search',
          `${config.dotComModule.url}/wp-json/wp/v2/tags`
        ).get()
      }

      service.searchPages = searchTerm => {
        return Restangular.oneUrl(
          'main.search',
          `${config.dotComModule.url}/wp-json/wp/v2/pages?search=${searchTerm}`
        ).get()
      }

      // Wordpress mobile menus
      service.mobileSideNavItems = []
      service.mobileSideNavItemsPromiseArray = []

      const arrayOfPromisesOfMenuApiCalls = () => {
        let filteredWordpressMenuItems

        // Reset the mobile nav items array if run again to prevent double nav
        // This function should only run on page load or login status change
        service.mobileSideNavItemsPromiseArray = []

        // If in cordova mode, filter out wordpress menu items not to be shown for native, if in web mode, filter out items not to be shown for web.
        if (mobileService.isMobileApp()) {
          filteredWordpressMenuItems = config.dotComModule.mobileNav.wordpressMenuItems.filter(
            item => item.showNative
          )
        } else {
          filteredWordpressMenuItems = config.dotComModule.mobileNav.wordpressMenuItems.filter(
            item => item.showWeb
          )
        }

        // Create array wordpress menu

        filteredWordpressMenuItems.forEach(item => {
          // Only add non-secure menu items to the array if the user is in trial mode
          if (auth.user.status === 'trial') {
            if (!config.dotComModule.mobileNav.secureWordpressMenus.includes(item.api)) {
              service.mobileSideNavItemsPromiseArray.push(service.getMobileMenuItem(item.api))
            }
          } else {
            // Add all menu items to the array if the user is logged in
            service.mobileSideNavItemsPromiseArray.push(service.getMobileMenuItem(item.api))
          }
        })

        return $q.all(service.mobileSideNavItemsPromiseArray)
      }

      const showInstacartDeliveryLink = store => {
        if (store.amenities && store.amenities !== null) {
          if (store.amenities.includes(config.dotComModule.mobileNav.amenities)) {
            // eslint-disable-next-line no-unused-expressions
            service.mobileSideNavItems
          } else {
            service.mobileSideNavItems = service.mobileSideNavItems.filter(
              item => item.name !== 'sidenav-mobile-amenities'
            )
          }
        } else {
          service.mobileSideNavItems = service.mobileSideNavItems.filter(
            item => item.name !== 'sidenav-mobile-amenities'
          )
        }
      }

      service.getWordpressMenuItems = () => {
        // Memoing this call as its very expensive, but re-run if logged in status changes
        if (!service.memoGetWordpressMenuItems || auth.user.status !== loggedInStatus) {
          loggedInStatus = auth.user.status
          service.memoGetWordpressMenuItems = $q((resolve, reject) => {
            arrayOfPromisesOfMenuApiCalls()
              .then(menuItems => {
                service.mobileSideNavItems = menuItems
                showInstacartDeliveryLink(auth.user.store)
              })
              .then(() => {
                if (service.mobileSideNavItems.length > 0) {
                  resolve(service.mobileSideNavItems)
                } else {
                  $rootScope.$broadcast('sidenav:fallback')
                  reject('Error, no Menus Provided')
                }
              })
              .catch(() => {
                reject('Error resolving Wordpress menus')
              })
          })
        }
        return service.memoGetWordpressMenuItems
      }
    }
  )
