import angular from 'angular'
import template from './cellOverlay.html'

export default angular
  .module('CellOverlayComponentModule', [])
  .directive('cellOverlay', function () {
    return {
      scope: {
        showIf: '=',
      },
      replace: true,
      transclude: true,
      template,
      controller($scope, $element) {
        $scope.close = function () {
          $scope.showIf = null
        }

        // Add a class to the parent scope's element if the overlay is open
        // This is used to hide the promo tag

        $scope.$watch('showIf', (newVal, oldVal) => {
          if (newVal !== oldVal) {
            $element.parent().toggleClass('overlay-open', !!$scope.showIf)
          }
        })
      },
    }
  })
