import angular from 'angular'

export default angular
  .module('InStoreComponentModule', [])
  .config(($ocLazyLoadProvider, $stateProvider) => {
    $stateProvider

      .state('main.inStoreList', {
        abstract: true,
        url: '/list',
        onEnter($state, auth, config, oneCartService, utilService) {
          // If a user goes into an inStoreList state in a non-instore mode, send them to cart review.
          if (auth.getContext().intent !== 'instore') {
            // Using a hard refresh here to reload the homepage as we need a reload to properly reset the context intent and to not trigger another $stateChangeStart event.
            const lastState = utilService.getLastState()
            const lastStateName = lastState && lastState.name
            if (oneCartService.enabled() && config.oneCart.listReviewToCartRedirectUrl) {
              $state.go(config.oneCart.listReviewToCartRedirectUrl, { open_sidecart: true })
            } else {
              $state.go(
                !lastStateName || !lastStateName.match('main.checkout')
                  ? 'main.checkout.cart'
                  : 'main.home'
              )
            }
          }
        },
      })

      .state('main.inStoreList.review', {
        url: '/review',

        views: {
          'content@': {
            template: require('./review/inStoreListReview.html'),
            controller: 'InStoreListReviewController',
            controllerAs: '$ctrl',
          },
        },
        resolve: {
          loadCtrl: $ocLazyLoad => {
            return import(
              /* webpackChunkName: "inStoreListReviewController", webpackPrefetch: true */ './review/inStoreListReview.controller'
            )
              .then(mod => $ocLazyLoad.load(mod.default))
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },
          title(lang, titleService) {
            return titleService.setTitle(lang.values.reviewList)
          },
        },
      })
  })
