import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('categoryFilterComponentModule', [])

  // Hide links to categories which don't support one or more of the selected filters

  .directive('categoryFilter', $state => {
    return {
      restrict: 'A',
      scope: {
        categoryFilter: '=',
      },
      link(scope, el) {
        if ($state.params.tag) {
          let tags
          if (!angular.isArray($state.params.tags)) {
            tags = [$state.params.tags]
          } else {
            tags = $state.params.tags
          }
          // eslint-disable-next-line eqeqeq
          const found = _.intersection(tags, scope.categoryFilter.tags).length == tags.length
          if (!found) {
            el[0].style.display = 'none'
          }
        }
      },
    }
  })
