import angular from 'angular'
import { PAYMENT_CONTEXTS } from '@app/angular/constants/payments'
import {
  MEMBERSHIP_STATUSES,
  EXPRESS_SUBSCRIPTION_PLAN_TERMS,
} from '@app/angular/constants/express'
import template from './icExpressForm.html'

class IcExpressForm {
  constructor($rootScope, $scope, errorModalService, express, lang) {
    Object.assign(this, {
      $rootScope,
      $scope,
      additionalFormText: '',
      errorModalService,
      express,
      isSubscribing: false,
      lang,
    })

    $scope.payment = {
      auth_capture: null,
    }

    $scope.paymentContext = PAYMENT_CONTEXTS.EXPRESS
  }

  $onInit() {
    const { whyProvidePaymentsNow } = this.lang.values

    this.additionalFormText =
      this.userSubscriptionStatus === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE
        ? `${whyProvidePaymentsNow} <i class="icon-info"></i>`
        : ''
  }

  /**
   * Subscribes the user to an Express membership
   *
   * @returns { Promise }
   */
  async subscribe() {
    this.isSubscribing = true
    const { ext_id, ext_payer_id } = this.$scope.payment.auth_capture.payment_instrument

    let subscriptionPlanId = this.planIdToSubscribeTo
    if (!subscriptionPlanId) {
      subscriptionPlanId =
        this.userSubscriptionStatus === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE
          ? await this.express.getPlanIds(EXPRESS_SUBSCRIPTION_PLAN_TERMS.TRIAL)
          : await this.express.getPlanIds(EXPRESS_SUBSCRIPTION_PLAN_TERMS.YEARLY)
    }

    const payload = {
      ext_id,
      ext_payer_id,
      subscription_plan_id: subscriptionPlanId,
    }

    if (this.orderId) {
      payload.order_id = this.orderId
    }

    const result = await this.express.subscribe(payload)
    if (result.error) {
      this.handleSubscriptionError(result.error)
    } else {
      this.onSubscriptionSuccess()
    }
  }

  handleSubscriptionError(error) {
    const { errorModalService, lang } = this

    errorModalService.open(
      error?.data?.message ||
        lang.templateAsString(lang.values.defaultErrorMessageSuffix, {
          error: lang.values.tryAgainPolite,
        }),
      lang.values.ok
    )
  }
}

export default angular.module('IcExpressFormComponentModule', []).component('icExpressForm', {
  bindings: {
    enableRetailerTheme: '<',
    onSubscriptionSuccess: '&',
    onCancelFormClick: '&',
    planIdToSubscribeTo: '<',
    userSubscriptionStatus: '<',
    orderId: '<?',
  },
  template,
  controller: IcExpressForm,
  controllerAs: '$ctrl',
})
