import angular from 'angular'
import isMobileContainerUrl from '@app/utils/isMobileContainerUrl'
import urlParser from '@app/utils/urlParser'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'

export default angular
  .module('redirectServicesModule', [])
  .service(
    'redirectService',
    function canvas(
      $http,
      $location,
      $state,
      $stateParams,
      $window,
      config,
      localStorageService,
      mobileService,
      utilService
    ) {
      const isDotComHostname = hostname =>
        config.dotComModule.url && urlParser(config.dotComModule.url).hostname === hostname

      // Only allow redirecting to the CMS/DotCom urls.
      const isRedirectUrlValid = url => {
        const { protocol, hostname } = urlParser(url)
        return protocol === 'https:' && config.dotComModule?.enable && isDotComHostname(hostname)
      }

      // Save url or app state for redirect back from third party site
      this.saveRedirectUrl = (
        url,
        { state, stateParams } = { state: $state, stateParams: $stateParams }
      ) => {
        const redirectUrl = {}

        if (url && isRedirectUrlValid(url)) {
          redirectUrl.href = url
        } else {
          redirectUrl.state = state?.name || state?.current?.name
          redirectUrl.params = stateParams
        }

        localStorageService.deprecatedSet('redirectUrl', redirectUrl)
      }

      this.loadRedirectUrl = defaultState => {
        const redirectUrl = localStorageService.deprecatedGet('redirectUrl')
        localStorageService.deprecatedRemove('redirectUrl')
        return this.goToRedirectUrl(redirectUrl, defaultState)
      }

      this.goToRedirectUrl = (redirectUrl, defaultState) => {
        if (redirectUrl && redirectUrl.href && isRedirectUrlValid(redirectUrl.href)) {
          if (mobileService.isMobileApp() && isMobileContainerUrl(redirectUrl)) {
            utilService.openExtLink(redirectUrl.href)
          } else {
            $window.open(redirectUrl.href, '_self')
          }
          return Promise.resolve()
        }
        if (redirectUrl && redirectUrl.state) {
          return $state.go(redirectUrl.state, redirectUrl.params, { reload: true })
        }
        if (defaultState && defaultState.state) {
          return $state.go(defaultState.state, defaultState.params, { reload: true })
        }
        return false
      }

      this.hasValidRedirectUrl = () => {
        const redirectUrl = localStorageService.deprecatedGet('redirectUrl')
        return !!(redirectUrl && redirectUrl.href && isRedirectUrlValid(redirectUrl.href))
      }

      eventEmitter.on(EVENT_TYPES.SET_REGISTRATION_URL, this.saveRedirectUrl)
    }
  )
