import angular from 'angular'
import { CART_TYPES } from '@app/angular/constants/cart'
import { IC_PLACEMENTS } from '@app/angular/constants/express'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { imageTransform } from '@instacart/enterprise-services'

import { utils as timeslotUtils } from '@instacart/enterprise-timeslots'

export default angular
  .module('CheckoutConfirmationControllerModule', [])
  .controller(
    'CheckoutConfirmationController',
    function (
      $rootScope,
      $scope,
      $state,
      api,
      auth,
      canvas,
      cartService,
      checkoutService,
      config,
      eComLightService,
      express,
      lang,
      mobileService,
      modifiedProgressSegments,
      order,
      placementsService,
      rateApp,
      reservationService,
      utilService
    ) {
      const { isMobile } = mobileService
      const isPickup = order.fulfillment_type === 'pickup'
      const postCheckoutPlacementEnabled = express.isPlacementConfigEnabled(
        IC_PLACEMENTS.POST_CHECKOUT
      )
      $scope.imageTransform = imageTransform

      Object.assign(
        $scope,
        {
          config,
          isMobile,
          isPickup,
          order,
          postCheckoutPlacementEnabled,
          utilService,
          cartService,
        },
        {
          delivery: { location: {}, marker: {} },
          isFromNewCheckout: config.newCheckoutFlow.checkoutPage.enabled,
          isSubscribed: false,
          isSubscribing: false,
          mapTitle: isPickup ? order.store.name : order.delivery_address.address1,
          orderObject: isPickup ? order.store.address : order.delivery_address,
          progressSegments: modifiedProgressSegments,
          userCurrentSubscription: null,
          icExpressSavingAmount: isPickup
            ? order.estimated_totals.pickup_fee
            : order.estimated_totals.delivery_fee,

          expressOrderSuccessText: lang.template(lang.values.orderSuccessNewExpressDelivery, {
            retailer: lang.values.clientName,
            expressName: config.icExpress.name,
          }),

          onClickIcExpress: () => {
            $scope.isSubscribing = true
            express.handleSubscribe(
              checkoutService.getLastUsedPaymentInstrument(),
              order.id,
              subscription => {
                $scope.isSubscribing = false
                $scope.isSubscribed = express.hasSubscription()
                $scope.userCurrentSubscription = subscription
              }
            )
          },
        }
      )

      $scope.getInstructions = () => {
        const shoppingMode = eComLightService.inEComLightMode
          ? SHOPPING_MODES.CATERING
          : auth.getContext().intent

        const orderInstructions = config.orderInstructions[shoppingMode]

        if (!orderInstructions?.enabled) return

        return orderInstructions
      }

      // Fetches user current express subscription if necessary, and passes it down to components
      if (postCheckoutPlacementEnabled) {
        express
          .getCurrentSubscription()
          .then(subscription => ($scope.userCurrentSubscription = subscription))
      }

      // On init of this controller, we assume an order has been successfully placed,
      //  we must therefore fetch the newly empty cart, and the cleared reservation
      cartService.fetch().then(() => {
        const orderCartType = order.ext_data.source_cart_type
        $scope.remainingCartType = cartService
          .getCartTypes()
          .filter(cartType => cartType !== orderCartType)[0]
        $scope.remainingCarts = cartService.getCartTypeToItemsMap()
      })
      reservationService.fetch()

      if (config.mobileAppRate.callOnCheckout) {
        // Call mobile service to prompt an app review if criteria are met
        rateApp.promptAppReview()
      }
      // Broadcast for use in custom js tags.
      $rootScope.$broadcast('orderConfirmation', $scope.order.plain())

      $scope.updateContextAndRedirectToCheckout = () => {
        const shoppingMode =
          $scope.remainingCartType === CART_TYPES.CATERING
            ? SHOPPING_MODES.CATERING
            : SHOPPING_MODES.PICKUP

        auth.setIntent(shoppingMode).finally(() => $state.go('main.checkout.checkoutPage'))
      }

      placementsService.getOrderConfirmationPlacement(isMobile).then(response => {
        if (response && response[0]) {
          const firstPlacement = response[0]

          canvas.logImpression(firstPlacement)
          $scope.orderConfirmationPlacement = firstPlacement
        }
      })

      $scope.handleImagePlacementClick = placement =>
        placementsService.handleImagePlacementClick(placement, 'order confirmation')

      $scope.timeslotLabelFn = () => {
        return timeslotUtils.getMainLabel($scope.order, {
          verboseLevel: 2,
          format: config.apiDateFormat,
        })
      }

      // If the new checkout is enabled and is delivery we have to
      // fetch the delivery address location in order to draw a
      // marker on the delivery map
      if ($scope.isFromNewCheckout && !$scope.isPickup) {
        const address = `${$scope.orderObject.address1} ${$scope.orderObject.province}`

        api.getGeocodeAddress(address).then(response => {
          const {
            data: { features: locations },
          } = response
          const [longitude, latitude] = locations?.[0]?.center
          const location = { longitude, latitude }

          if (longitude && latitude) {
            $scope.delivery.marker.id = 'delivery-map-address'
            $scope.delivery.marker.location = location
            $scope.delivery.location = location
          }
        })
      }
    }
  )
  .run($templateCache => {
    $templateCache.put(
      'checkoutConfirmation.new.html',
      require('./views/checkoutConfirmation.new.html')
    )
  })
