/**
 * Provides a service that listens for UnataComm events in the background wherever
 * and executes any callbacks specified for that event.
 * This service is useful for attaching handlers for any UnataComm events without requiring
 * any components to be on the page.
 */
import angular from 'angular'

export default angular
  .module('UnataCommInterceptorServicesModule', [])
  .service(
    'unataCommInterceptorService',
    function ($filter, $window, auth, config, reservationService, unataCommService) {
      const service = this

      // Define handlers
      service.sendAdditionalContextDetails = () => {
        const { user } = auth

        const shoppingContext = user?.context?.intent || 'instore'
        const storeName = user?.store?.name

        if (shoppingContext === 'delivery' || shoppingContext === 'pickup') {
          const reservation = reservationService.displayReservation

          if (!reservation) {
            const errorPayload = {
              message: 'Error encountered while retrieving the reservation',
              detail: 'Reservation not found',
            }
            const payload = {
              context: shoppingContext,
              error: errorPayload,
            }

            unataCommService.send({
              eventType: unataCommService.EventType.ADDITIONALCONTEXTDETAILS,
              payload,
            })
          } else {
            // return an object with the required details
            let detailString
            // eslint-disable-next-line eqeqeq
            if (shoppingContext == 'delivery') {
              detailString = `${$filter('time')(reservation.timeslot.from_time)}-${$filter('time')(
                reservation.timeslot.to_time
              )}`
            } else {
              detailString = `${$filter('date')(
                reservation.fulfillment_date,
                'MMM D',
                config.apiDateFormat
              )}, ${$filter('time')(reservation.timeslot.from_time)}-${$filter('time')(
                reservation.timeslot.to_time
              )}`
            }

            const payload = {
              context: shoppingContext,
              details: {
                full_datetime: reservation.fulfillment_date,
                date: `${$filter('date')(
                  reservation.fulfillment_date,
                  'YYYY-MM-DD',
                  config.apiDateFormat
                )}`,
                from_time: reservation.timeslot.from_time,
                to_time: reservation.timeslot.to_time,
                detail_string: detailString,
              },
            }
            unataCommService.send({
              eventType: unataCommService.EventType.ADDITIONALCONTEXTDETAILS,
              payload,
            })
          }
        } else {
          const payload = {
            context: shoppingContext,
            details: {
              store: storeName,
              detail_string: storeName ? `${config.lang.atStore} ${storeName}` : '',
            },
          }
          unataCommService.send({
            eventType: unataCommService.EventType.ADDITIONALCONTEXTDETAILS,
            payload,
          })
        }
      }

      // Handles debug toggle events. Use browser local storage to align with cms-widgets
      service.handleDebugEvents = event => {
        const debugMode = event.data.payload
        const DEBUG_STORAGE_KEY = 'DEBUG_UNATA_COMM'

        if (debugMode) {
          $window.localStorage.setItem(DEBUG_STORAGE_KEY, debugMode)
          return
        }

        $window.localStorage.removeItem(DEBUG_STORAGE_KEY)
      }
    }
  )
