import angular from 'angular'
import template from './storesMap.html'

const mapHasPickup = require('static/images/map-pin-hasPickup.png')
const mapPinActive = require('static/images/map-pin-active.png')

const MAP_INITIAL_POSITION = ['-95.665', '37.6']
const MAP_STYLE = 'mapbox://styles/mapbox/streets-v9'
const MAP_ZOOM = 7
const MAP_ZOOM_SELECTED = 10

class StoreMap {
  constructor($scope, $timeout, config, lang) {
    this.config = config
    this.$scope = $scope
    this.map = {}
    this.setupMapBox()
    this.lang = lang
    this.$timeout = $timeout
  }

  $onInit() {
    // Ensures that the HTML container exists
    this.$timeout(() => {
      this.initializeMap()

      this.$scope.$watch(
        () => this.currentLocation,
        () => this.centralize()
      )

      this.$scope.$watch(
        () => this.selectedStore,
        () => {
          this.clearMarkers()
          this.drawStores()
        }
      )

      this.$scope.$watch(
        () => this.stores,
        () => {
          this.clearMarkers()
          this.drawStores()
        }
      )
    })
  }

  /**
   * Centralize the map to the currentLocation coordinates
   *
   * @returns { undefined }
   */
  centralize() {
    if (this.currentLocation) {
      const { latitude, longitude } = this.currentLocation

      this.map.flyTo({
        center: [longitude, latitude],
        zoom: this.initialZoom || MAP_ZOOM_SELECTED,
      })
    }
  }

  /**
   * Configure mapboxgl with the accessToken
   *
   * @returns { undefined }
   */
  setupMapBox() {
    if (window.mapboxgl) {
      window.mapboxgl.accessToken = this.config.mapboxglAccessToken
    }
  }

  /**
   * Initialized the map by creating a canvas inside the given *mapId* element
   *
   * @returns { undefined }
   */
  initializeMap() {
    const { latitude, longitude } = this.currentLocation || {}
    const center = this.currentLocation ? [longitude, latitude] : MAP_INITIAL_POSITION

    if (window.mapboxgl) {
      this.map = new window.mapboxgl.Map({
        center,
        container: this.mapId,
        style: MAP_STYLE,
        zoom: this.initialZoom || MAP_ZOOM,
      })

      this.map.addControl(new window.mapboxgl.NavigationControl())
    }
  }

  /**
   * Draw store markers on the map
   *
   * @returns { undefined }
   */
  drawStores() {
    this.stores.forEach(store => {
      const { latitude, longitude } = store.location

      if (latitude && longitude && window.mapboxgl) {
        const el = this.createMarkerForStore(store)
        new window.mapboxgl.Marker(el) //eslint-disable-line
          .setLngLat([longitude, latitude])
          .addTo(this.map)
      }
    })
  }

  /**
   * Creates a marker element for a given store
   * and attach a click event handler to it
   *
   * @param { object } store
   *
   * @returns { object } HTML object
   */
  createMarkerForStore(store) {
    const markerElement = document.createElement('div')
    const icon =
      this.selectedStore && this.selectedStore.id === store.id ? mapPinActive : mapHasPickup

    markerElement.id = `store-marker-${store.id}`
    markerElement.className = 'marker'
    markerElement.style.width = '30px'
    markerElement.style.height = '40px'
    markerElement.style.backgroundImage = `url('${icon}')`

    markerElement.addEventListener(
      'click',
      (store => {
        const { latitude, longitude } = store.location

        return () => {
          this.onStoreSelect({ store })
          this.clearMarkers()
          this.drawStores()
          this.map.flyTo({
            center: [longitude, latitude],
          })
        }
      })(store)
    )

    return markerElement
  }

  /**
   * Removes all markers from the map
   */
  clearMarkers() {
    document.querySelectorAll(`#${this.mapId} .marker`).forEach(el => el.remove())
  }
}

export default angular.module('StoresMapModule', []).component('storesMap', {
  bindings: {
    mapId: '@',
    initialZoom: '<',
    stores: '<',
    selectedStore: '<',
    currentLocation: '<',
    onStoreSelect: '&',
  },
  template,
  controller: StoreMap,
})
