import angular from 'angular'
import AppRatePlugin from '@app/react/mobile/plugins/appRatePlugin'
import isCapacitor from '@app/utils/isCapacitor'

export default angular
  .module('rateAppServicesModule', [])
  .service(
    'rateApp',
    function mobileService($window, config, localStorageService, mobileService, utilService) {
      const service = this

      const LOCAL_STORAGE_RATE_APP = 'rateapp'

      const isMobileApp = mobileService.isMobileApp()

      let rateAppLocalStorage = {
        remindagain: null,
        rated: null,
      }

      const mobileRateAppAvailable = () => {
        return (
          isMobileApp &&
          window.AppRate !== undefined &&
          config.mobileAppRate &&
          config.mobileAppRate.enabled
        )
      }

      const getRateAppLocalStorage = () => {
        if (localStorageService.deprecatedGet(LOCAL_STORAGE_RATE_APP)) {
          rateAppLocalStorage =
            localStorageService.deprecatedGet(LOCAL_STORAGE_RATE_APP) || rateAppLocalStorage
        }
      }

      const checkRemindAgain = () => {
        getRateAppLocalStorage()
        const remindAgain = new Date(rateAppLocalStorage.remindagain) || null
        const nowDate = new Date()
        return remindAgain < nowDate
      }

      const remindAgain = () => {
        // set localstorage with todays date + configured number of days
        const remindAgain = utilService.addDays(new Date(), config.mobileAppRate.remindAgain)
        rateAppLocalStorage.remindagain = remindAgain
        localStorageService.deprecatedSet(LOCAL_STORAGE_RATE_APP, rateAppLocalStorage)
      }

      const setupAppReview = () => {
        // configure App Rate plugin
        window.AppRate.preferences = {
          displayAppName: config.mobileAppRate.displayAppName,
          usesUntilPrompt: config.mobileAppRate.usesUntilPrompt,
          promptAgainForEachNewVersion: config.mobileAppRate.promptAgainForEachNewVersion,
          inAppReview: config.mobileAppRate.inAppReview,
          simpleMode: config.mobileAppRate.simpleMode,
          storeAppURL: {
            ios: config.mobileAppRate.appleAppId,
            android: `market://details?id=${config.mobileAppRate.androidPackageName}`,
          },
          customLocale: {
            title: config.mobileAppRate.customLocale.title,
            message: config.mobileAppRate.customLocale.message,
            cancelButtonLabel: config.mobileAppRate.customLocale.cancelButtonLabel,
            laterButtonLabel: config.mobileAppRate.customLocale.laterButtonLabel,
            rateButtonLabel: config.mobileAppRate.customLocale.rateButtonLabel,
            yesButtonLabel: config.mobileAppRate.customLocale.yesButtonLabel,
            noButtonLabel: config.mobileAppRate.customLocale.noButtonLabel,
            appRatePromptTitle: config.mobileAppRate.customLocale.appRatePromptTitle,
            feedbackPromptTitle: config.mobileAppRate.customLocale.feedbackPromptTitle,
          },
          // callbacks are currently required in this version of apprate or it will throw errors.
          callbacks: {
            handleNegativeFeedback() {
              // should maybe have the option to do something with this. send an email, etc.
            },
            onRateDialogShow(_callback) {
              // currently this is not working in the installed version of the plugin
              // callback(1) // cause immediate click on 'Rate Now' button
            },
            onButtonClicked(buttonIndex) {
              switch (buttonIndex) {
                case 2:
                  // remind me later
                  remindAgain()
                  break
              }
            },
          },
        }
      }

      document.addEventListener('deviceready', () => {
        if (mobileRateAppAvailable()) {
          setupAppReview()
        }
      })

      // prompt for the app review
      service.promptAppReview = () => {
        if (checkRemindAgain()) {
          if (isCapacitor()) {
            AppRatePlugin.promptForRating()
            // Call `remindAgain` to set a localStorage val to prevent requesting a review too soon.
            remindAgain()
          } else if (mobileRateAppAvailable()) {
            // don't call the prompt until after the 'remind me again' date
            window.AppRate.promptForRating(config.mobileAppRate.callRateDialogImmediately)
          }
        }
      }
    }
  )
