import { findItemInList } from '../userList/utils'

type FormatPriceProps = {
  price: number
  quantity: number
  priceWithNumberTemplate: string
  unit?: string
}

const formatPrice = ({
  price,
  quantity,
  priceWithNumberTemplate,
  unit,
}: FormatPriceProps): string => {
  const priceString = formatCurrency(price)

  if (quantity > 1) {
    const qty = unit === 'ea' || !unit ? `${quantity}` : `${quantity} ${unit}`
    return interpolate(priceWithNumberTemplate, { price: priceString, quantity: qty })
  }

  if (unit) return `${priceString} /${unit}`

  return priceString
}

// TODO: The current implementation of this selector is a placeholder for when
// we source products from Redux.
export const productSelector = (_state: unknown, props: Record<'product', StoreProduct>) =>
  props.product

/**
 * Returns the associated ShoppingListItem object, if one exists in the cart.
 * TODO: This selector needs to be updated. Right now it expects you to feed it
 * the ShoppingListItem because right now the shopping cart is not in the main redux
 * store. It's a bit of a hack/placeholder for future implementation. It exists to allow
 * other selectors to use it already.
 */
export const shoppingCartItemSelector = (
  _state: unknown,
  props: Record<'cartItem', ShoppingListItem | undefined>
) => props.cartItem

export const hasLoyaltyCardSelector = (_state: unknown, props: Record<'hasLoyaltyCard', boolean>) =>
  props.hasLoyaltyCard

export const promoDiscountSelector = createSelector(
  productSelector,
  configSelector,
  hasLoyaltyCardSelector,
  langSelector,
  isMobileSelector,
  // eslint-disable-next-line max-params
  (product, config, hasLoyaltyCard, lang, isMobile): PromoMarkerProps | undefined => {
    const { loyalty_price, promo_tag, sale_price, sale_end_date, loyalty_end_date } = product
    const { enableLoyaltyPricing, showSaleEndDate } = config

    const result: PromoMarkerProps = {
      isLoyalty: false,
      text: '',
    }

    if (promo_tag) {
      if (enableLoyaltyPricing && loyalty_price) {
        result.isLoyalty = true

        if (hasLoyaltyCard) {
          result.text = promo_tag as string

          if (showSaleEndDate && !isMobile && loyalty_end_date) {
            result.tooltipText = interpolate(lang.saleEndDate, {
              endDate: dayjs(loyalty_end_date).format('MMM D'),
            })
          }
        } else {
          result.text = lang.loyaltyPricing.tag

          if (!isMobile) {
            result.tooltipText = interpolate(lang.loyaltyPricing.tagTooltip, { promo: promo_tag })
          }
        }

        return result
      }
      if (sale_price) {
        result.text = promo_tag as string

        if (showSaleEndDate && !isMobile && sale_end_date) {
          result.tooltipText = interpolate(lang.saleEndDate, {
            endDate: dayjs(sale_end_date).format('MMM D'),
          })
        }

        return result
      }
    }

    return undefined
  }
)

export const promoOfferSelector = createSelector(
  productSelector,
  configSelector,
  langSelector,
  // eslint-disable-next-line max-params
  (product, config, lang): PromoMarkerProps | undefined => {
    const { offer_title, offers } = product
    const { enableOfferBadgeTypes, enableOfferBadge } = config

    const result: PromoMarkerProps = {
      isLoyalty: false,
      text: '',
    }

    if (offer_title?.length) {
      result.text = offer_title

      return result
    }

    const thereAreSupportedOffers = offers?.some(offer =>
      enableOfferBadgeTypes.includes(offer.offer_type)
    )
    if (enableOfferBadge && thereAreSupportedOffers) {
      result.text = lang.offerInside

      return result
    }

    return undefined
  }
)

export const promotionsSelector = createSelector(
  promoDiscountSelector,
  promoOfferSelector,
  (discount, offer): PromoMarkerProps[] => [
    ...(discount ? [discount] : []),
    ...(offer ? [offer] : []),
  ]
)

/**
 * Returns whether or not UOM should be displayed for a given product
 * Takes into account possible whitelist in the config
 */
export const shouldShowUOMSelector = createSelector(
  productSelector,
  configSelector,
  (product, config) => {
    const uom = product?.uom_price?.uom || ''
    const whitelist = config.displayPriceUnitOfMeasurementAllowlist

    // If the white list is null, consider all uoms ok
    const enabled = whitelist === null || whitelist?.includes(uom)
    return Boolean(enabled && uom)
  }
)

/**
 * Returns current price per unit, e.g. $0.39/oz
 */
export const pricePerUnitStringSelector = createSelector(
  shouldShowUOMSelector,
  productSelector,
  (enabled, product) => {
    if (!enabled) return undefined
    if (!product.uom_price) return undefined

    const { price, uom } = product.uom_price

    return `${formatCurrency(price)}/${uom}`
  }
)

/**
 * Returns regular/base price without any discounts of promos
 */
export const regularPriceSelector = createSelector(productSelector, product => product.base_price)

/**
 * Returns formatted regular price string
 */
export const regularPriceStringSelector = createSelector(
  productSelector,
  langSelector,
  (product, lang) => {
    const { base_price, display_uom, base_quantity } = product

    return formatPrice({
      price: base_price,
      priceWithNumberTemplate: lang.priceWithQuantity,
      quantity: base_quantity,
      unit: display_uom,
    })
  }
)

/**
 * returns loyalty loyalty price, if set
 */
export const loyaltyPriceSelector = createSelector(
  productSelector,
  product => product.loyalty_price || undefined
)

/**
 * Returns formatted loyalty price, if set
 */
export const loyaltyPriceStringSelector = createSelector(
  productSelector,
  langSelector,
  (product, lang) => {
    if (!product.loyalty_price) return

    const { loyalty_price, loyalty_quantity, display_uom } = product

    return formatPrice({
      price: loyalty_price,
      priceWithNumberTemplate: lang.priceWithQuantity,
      quantity: loyalty_quantity || 1,
      unit: display_uom,
    })
  }
)

/**
 * returns sale price, if set
 */
export const salePriceSelector = createSelector(
  productSelector,
  product => product.sale_price || undefined
)

/**
 * returns formatted sale price, if set
 */
export const salePriceStringSelector = createSelector(
  productSelector,
  langSelector,
  (product, lang) => {
    const { sale_price, sale_quantity, display_uom } = product

    if (!sale_price) return

    return formatPrice({
      price: sale_price,
      priceWithNumberTemplate: lang.priceWithQuantity,
      quantity: sale_quantity || 1,
      unit: display_uom,
    })
  }
)

/**
 * Returns the current price which in the following order:
 * 1) Loyalty Price
 * 2) Sale Price
 * 3) Regular Price
 */
export const currentPriceSelector = createSelector(
  regularPriceSelector,
  salePriceSelector,
  loyaltyPriceSelector,
  (regularPrice, salePrice, loyaltyPrice): number => {
    return loyaltyPrice || salePrice || regularPrice
  }
)

/**
 * Returns the formatted current price
 */
export const currentPriceStringSelector = createSelector(
  regularPriceStringSelector,
  salePriceStringSelector,
  loyaltyPriceStringSelector,
  (regularPrice, salePrice, loyaltyPrice) => {
    return loyaltyPrice || salePrice || regularPrice
  }
)

/**
 * Returns whether or not the product has promo pricing,
 * e.g. sale or loyalty pricing. Does not take other offers
 * into consideration
 */
export const hasPromoPriceSelector = createSelector(
  loyaltyPriceSelector,
  salePriceSelector,
  (loyalty, sale) => Boolean(loyalty || sale)
)

/*
 * Indicates that current price has confirmed discount that comes from sale promo type or
 * loyalty type + loyalty card on file
 */
export const discountedPriceIsConfirmedSelector = createSelector(
  promoDiscountSelector,
  hasLoyaltyCardSelector,
  (promo, hasLoyaltyCard): boolean =>
    (promo?.isLoyalty === true && hasLoyaltyCard) || promo?.isLoyalty === false
)

/**
 * Returns whether the product is available or out of stock
 */
export const isAvailableSelector = createSelector(
  productSelector,
  configSelector,
  (product, config) => {
    if (!config.enableOutOfStockUIBehaviour) {
      return true
    }
    const stockLevel = product.availability?.stock_level

    return stockLevel !== StockLevels.OUT_OF_STOCK
  }
)

/**
 * Returns whether the product can be customized
 */
export const isCustomizableSelector = createSelector(productSelector, product =>
  Boolean(product.product_config?.options?.length)
)

/**
 * Check against config, if the item can be added to cart regardless of its stock level
 */
export const allowOutOfStockAddSelector = createSelector(
  authService.selectors.getUserIntent,
  configSelector,
  (intent, { enableOutOfStockAddToCartContexts }) =>
    Boolean(intent && enableOutOfStockAddToCartContexts?.includes(intent))
)

export const showPriceAlertSelector = createSelector(
  configSelector,
  authSelectors.getUser,
  (config, user): boolean =>
    !(
      config.disablePriceAlerts ||
      config.disablePriceAlertsOnProductCells ||
      !user?.status ||
      user?.status === 'trial'
    )
)

export const showSaveForLaterSelector = createSelector(
  configSelector,
  authSelectors.getUser,
  productSelector,
  (config, user, product) =>
    !(
      config.disableSaveForLaterLists ||
      product.product_config ||
      !user?.status ||
      user?.status === 'trial'
    )
)

/**
 * Returns the quantity of the product in the shopping cart
 */
export const quantityInCartSelector = createSelector(
  shoppingCartItemSelector,
  shoppingCartItem => shoppingCartItem?.quantity || 0
)

/**
 * Return maximum allowed quantity of the product in the cart
 */
export const maxQuantitySelector = createSelector(
  productSelector,
  product => product?.ext_data?.max_in_cart ?? product.ordering_parameters.max
)

/**
 * Returns minimum allowed quantity of the product in the cart
 */
export const minQuantitySelector = createSelector(
  productSelector,
  product => product.ordering_parameters.min
)

/**
 * Returns the quantity of product each cart add will increment by
 */
export const incrementQuantitySelector = createSelector(
  productSelector,
  product => product.ordering_parameters.increment
)

/**
 * Returns the quantity of product that will be
 * added to cart if no specific quantity is requested.
 * If the product is in the cart already, will be equal to
 * increment quantity. Otherwise, return minimum cart quantity
 */
export const defaultAddToCartQty = createSelector(
  incrementQuantitySelector,
  minQuantitySelector,
  quantityInCartSelector,
  (increment, min, qtyInCart) => {
    return qtyInCart > 0 ? increment : min
  }
)

export const orderByUnitSelector = createSelector(
  productSelector,
  product => product.ordering_parameters.display_name
)

export const ratingSelector = createSelector(
  productSelector,
  product => product.product_rating.average_rating
)

export const numberOfRatingsSelector = createSelector(
  productSelector,
  product => product.product_rating.user_count
)

export const sizeSelector = createSelector(
  productSelector,
  product => product.size_string || undefined
)

export const sizeWithPricePerUnitSelector = createSelector(
  sizeSelector,
  pricePerUnitStringSelector,
  (size, pricePerUnit) => {
    if (size && pricePerUnit) {
      return `${size} (${pricePerUnit})`
    }

    return size || pricePerUnit
  }
)

export const subscribedForPriceAlertSelector = createSelector(productSelector, product =>
  Boolean(product.price_alert)
)

/**
 * Returns whether or not the product is allowed
 * to be added to cart. Takes into account max order quantity
 * and product availability
 */
export const allowAddToCartSelector = createSelector(
  defaultAddToCartQty,
  maxQuantitySelector,
  isAvailableSelector,
  allowOutOfStockAddSelector,
  quantityInCartSelector,
  (_arg0: unknown, props: Record<'addToCartQty', number | undefined>) => props.addToCartQty,
  // eslint-disable-next-line max-params
  (defaultQtyToAdd, max, isAvailable, allowOOS, currentQty, qtyToAdd) => {
    const newQty = currentQty + (qtyToAdd || defaultQtyToAdd)
    return Boolean(newQty <= max && (isAvailable || allowOOS))
  }
)

export const allowDecrementCartQuantitySelector = createSelector(
  quantityInCartSelector,
  minQuantitySelector,
  (qty, min) => qty >= min
)

export const decrementCartQuantityType = createSelector(
  quantityInCartSelector,
  minQuantitySelector,
  (qty, min) => (qty > min ? 'subtract' : 'trash')
)

// TODO: Implement existing logic from Angular
export const titleSelector = createSelector(productSelector, langSelector, (product, lang) => {
  if (product.average_weight) {
    const averageWeight = interpolate(lang.avgWeight, {
      product: product.average_weight,
      unit: product.display_uom,
    })
    return `${product.name} (${averageWeight})`
  }

  return product.name
})

export const aisleSelector = createSelector(productSelector, product => product.aisle || undefined)

/**
 * addToCart Aria label
 */
export const addToCartLabelSelector = createSelector(
  authService.selectors.getUserIntent,
  langSelector,
  (intent, lang) => {
    if (intent === FulfillmentType.InStore) {
      return lang.addToListButtonLabel
    }
    // TODO: Modifying order
    // return $rootScope.modifyingOrder ? lang.addToOrderButtonLabel : lang.addToCartButtonLabel
    return lang.addToCartButtonLabel
  }
)

export const featuredLabelSelector = createSelector(
  configSelector,
  langSelector,
  (_state: unknown, props: Record<'featured', boolean>) => props.featured,
  (config, lang, isFeatured) => {
    if (!isFeatured || !config.showFeaturedLabel) return undefined

    return lang.featured
  }
)

/**
 * Returns the text for the "Sponsored" label
 * It could either be the default language or custom text depending on
 * who sponsored the product.
 */
export const sponsoredLabelSelector = createSelector(
  productSelector,
  configSelector,
  langSelector,
  (product, config, lang) => {
    if (!product.sponsored || !config.showSponsoredLabel || product.sponsoredLabel === false)
      return undefined

    if (product.sponsoredBy && Object.keys(lang.sponsoredBy).includes(product.sponsoredBy)) {
      return lang.sponsoredBy[product.sponsoredBy as keyof typeof lang.sponsoredBy]
    }

    return lang.sponsored
  }
)

export const previouslyPurchasedSelector = createSelector(productSelector, product =>
  product.tags.includes('purchased')
)

export const previouslyPurchasedLabelSelector = createSelector(
  previouslyPurchasedSelector,
  configSelector,
  (showLabel, config) => (showLabel ? config.buyItAgain.displayName : undefined)
)

/**
 * Returns the label text, e.g. "Featured" or "Buy it again".
 */
export const labelSelector = createSelector(
  featuredLabelSelector,
  sponsoredLabelSelector,
  previouslyPurchasedLabelSelector,
  (featured, sponsored, previouslyPurchased) => {
    return featured || sponsored || previouslyPurchased
  }
)

export const labelTypeSelector = createSelector(
  featuredLabelSelector,
  sponsoredLabelSelector,
  previouslyPurchasedLabelSelector,
  (featured, sponsored, previouslyPurchased) => {
    if (featured) return LabelTypes.Featured
    if (sponsored) return LabelTypes.Sponsored
    if (previouslyPurchased) return LabelTypes.Purchased
    return undefined
  }
)

// Item Tile-specific selectors

