import angular from 'angular'
import { shoppingList as shoppingListService } from '@instacart/enterprise-services'
import { getRemainingAmount } from '@instacart/enterprise-services/shopping-list/_common/utils'
import {
  getNumCartMinCount,
  getFirstCartMinMsgWithBanner,
} from '@instacart/enterprise-services/shopping-list/cart/utils'
import template from './sideCartBottomButtons.html'

const EBT_NOTICE_ALERT_ID = 'sidecart_noEBTMinimumText'

export default angular
  .module('SideCartBottomButtonsModule', [])
  .component('sideCartBottomButtons', {
    bindings: {
      closeSideCart: '&',
      handleCheckoutClick: '&',
      inStoreIntent: '<',
      showCartMinimumAlert: '<',
      showOrderOnlineButton: '<',
      mcoxCartData: '<',
    },
    template,
    transclude: true, // Allows for child content where ng-transclude is used within this template
    controller(
      $filter,
      $state,
      auth,
      cartService,
      config,
      ebtCardService,
      lang,
      mobileService,
      oneCartService,
      shoppingSelectorService
    ) {
      const { shoppingSelector } = config
      const currencyFilter = $filter('currency')

      Object.assign(this, {
        EBT_NOTICE_ALERT_ID,
        cartService,
        config,
        lang,
        mobileService,
        noEBTMinimum: lang.values.cartMinimum.noEBTMinimum,
        shoppingSelectorService,
      })

      this.isLoggedIn = () => auth.user.status !== 'trial'

      this.isEbtEnabled = () =>
        oneCartService.enabled() && config.ebt?.enabled && auth.user.store?.ebt_enabled_in_state

      this.isNotInstore = () => {
        return (
          !this.inStoreIntent &&
          (shoppingSelector.delivery.exists ||
            shoppingSelector.pickup.exists ||
            shoppingSelector.catering.exists)
        )
      }

      this.getMinCartAlertMessage = () => {
        const cartMinimum = shoppingListService.commonUtils.getMinimumCartSpend() || 0
        const cartTotals = cartService.getCartTotals()

        if (config.useMultiContextCart) {
          const cartMinCount = getNumCartMinCount(this.mcoxCartData)
          if (cartMinCount === 1) {
            return getFirstCartMinMsgWithBanner(this.mcoxCartData)
          }
          if (cartMinCount > 1) {
            return lang.template(lang.values.cartMinimum.mcoxCartMinGenericText, {
              amount: currencyFilter(cartMinimum),
            })
          }
        }

        return lang.template(lang.values.cartMinimum.miniCartAlertText, {
          amount: currencyFilter(cartMinimum),
          remainingAmount: currencyFilter(
            getRemainingAmount(
              cartMinimum,
              cartTotals?.pre_reward_program_discount_product_total,
              cartService?.cart?.items
            )
          ),
        })
      }

      this.getCheckoutButtonMessage = () => {
        const checkoutString = lang.values.checkoutLinkWithSubtotal || lang.values.checkoutLink
        const cartSubtotal = config.useMultiContextCart
          ? cartService.cart?.product_total
          : cartService.getCartTotalForCurrentType()
        return lang.template(checkoutString, {
          subtotal: currencyFilter(cartSubtotal),
        })
      }

      this.onBottomButtonActionClick = $event => {
        if (this.showCartMinimumAlert) {
          const { desktop, mobile } = config.continueShoppingRedirect
          const continueShoppingRedirect = mobileService.isMobile ? mobile : desktop

          $state.go(continueShoppingRedirect)
          this.closeSideCart()
        } else {
          this.handleCheckoutClick({ $event })
        }
      }
    },
  })
