import angular from 'angular'
import template from './storeList.html'

class StoreList {
  constructor($rootScope, config, utilService) {
    Object.assign(this, { $rootScope, config, utilService, currentStore: null })
  }

  handleButtonClick(store) {
    this.onStoreApply({ store })
  }

  handleRadioChange(store) {
    this.onStoreSelect({ store })
  }

  createStoreAddressString(store) {
    return `${store.address.address1}, ${store.address.province} ${store.address.postal_code}`
  }

  createFullStoreAddressString(store) {
    return `
        ${store.address.address1},
        ${store.address.address2 ? `, ${store.address.address2}` : ''}
        ${store.address.address3 ? `, ${store.address.address3}` : ''}
        ${store.address.city}, ${store.address.province} ${store.address.postal_code}
      `
  }
}

export default angular.module('StoreListModule', []).component('storeList', {
  template,
  bindings: {
    stores: '<',
    selectedStore: '<',
    applyButtonText: '<',
    onStoreSelect: '&',
    onStoreApply: '&',
  },
  controller: StoreList,
})
