import React, { ComponentType, Suspense } from 'react'
import { DeepPartial } from 'utility-types'
import { Tokens } from '@instacart/cocktail-design-tokens/tokens'
import { ErrorBoundary } from '@instacart/enterprise-ui-common'
import AppProvider from '@app/react/AppProvider'
import LazyLoadingIndicator, {
  Props as LazyLoadingIndicatorProps,
} from './lazyLoadingIndicator/LazyLoadingIndicator'
import ngIgnore from './ngIgnore'

type Component = ComponentType<{
  $document: [Document]
  theme: DeepPartial<Tokens>
}>

const reactWrapper = (
  BaseComponent: ComponentType,
  indicatorProps?: LazyLoadingIndicatorProps
): Component => {
  const Component = ngIgnore(BaseComponent)
  return ({ $document, theme, ...rest }) => (
    <AppProvider document={$document[0]} tokens={theme}>
      <ErrorBoundary fallback={<span className="icon-error" aria-hidden="true" />}>
        <Suspense fallback={<LazyLoadingIndicator {...indicatorProps} />}>
          <Component {...rest} />
        </Suspense>
      </ErrorBoundary>
    </AppProvider>
  )
}

export default reactWrapper
