import angular from 'angular'
import { isWaysToShopEnabled } from '@app/utils/isModuleEnabled'

export default angular
  .module('OfferDetailsControllerModule', [])
  .controller(
    'OfferDetailsController',
    function (
      $filter,
      $rootScope,
      $scope,
      $state,
      Restangular,
      analytics,
      cartService,
      config,
      offer,
      offerDetailsModalService,
      offersService,
      titleService
    ) {
      $scope.offer = offer

      const isOffersBreadcrumbEnabled = () => {
        return config.offersAndCoupons.showOffersBreadcrumbs
      }
      if (!offer) {
        $state.go('main.404')
      } else {
        let offerTypeString

        if ($scope.offer.offer_type === 'coupon') {
          offerTypeString = 'coupons'
        } else if ($scope.offer.offer_type === 'offer') {
          offerTypeString = 'offers'
        }

        $scope.breadcrumbs = [
          ...(!isWaysToShopEnabled()
            ? []
            : [
                {
                  label: config.lang.waysToShop,
                  state: 'main.shop',
                },
              ]),
          ...(!isOffersBreadcrumbEnabled()
            ? []
            : [
                {
                  label: config.lang[offerTypeString],
                  state: `main.shop.${offerTypeString}`,
                },
              ]),
          {
            label: offer.name,
            state: 'main.offers.details',
            params: {
              offerId: offer.id,
            },
          },
        ]

        $scope.isOffersDetailsPage = $state.current.name === 'main.offers.details'
        if ($scope.isOffersDetailsPage) {
          titleService.setTitle(config.lang.coupons)
        }

        $scope.getConditionLabel = condition_index => {
          const numProductRewards = $scope.rewards.filter(
            r => r.type === 'products' && r.product_details.length
          )
          if ($scope.conditions.length > 1 || numProductRewards > 0) {
            // Offers with multiple conditions or reward section should display shorter condition heading titles
            return (
              $scope.conditions[condition_index].cdn_collection_title ||
              $scope.offerProgressData.condition.collection_title
            )
          }
          // Offers with single condition and no reward section should display full heading title on condition heading
          return $scope.offer.cdn_collection_title
        }

        $scope.conditions = [offer.condition]
        if (offer.additional_cdns && offer.additional_cdns.length > 1) {
          $scope.conditions = offer.additional_cdns
        }

        $scope.rewards = [offer.reward]
        if (offer.additional_rwds && offer.additional_rwds.length > 1) {
          $scope.rewards = offer.additional_rwds
        }

        // eslint-disable-next-line eqeqeq
        // eslint-disable-next-line eqeqeq
        if ($scope.conditions.length == 1 && $scope.rewards.length == 1) {
          // Offers with one condition and reward  should go through old endpoint for
          // backwards compatibility
          if (offer.condition.type !== 'cart') {
            if (offer.condition.type !== 'collection') {
              Restangular.one('offers', offer.id)
                .all('condition_products')
                .getList({ limit: 5 })
                .then(response => {
                  $scope.conditions[0].product_details = response
                })
            }
          }
          if (offer.reward.type === 'products') {
            Restangular.one('offers', offer.id)
              .all('reward_products')
              .getList({ limit: 5 })
              .then(response => {
                $scope.rewards[0].product_details = response
              })
          }
        } else {
          // Newer offers with multiple condition should call the v2 endpoint
          Restangular.one('offers', offer.id)
            .all('v2/products')
            .getList({ limit: 5 })
            .then(response => {
              $scope.conditions.forEach((condition, i) => {
                condition.product_details = response.cdn_products[i].items
              })
              $scope.rewards.forEach((reward, i) => {
                reward.product_details = response.rwd_products[i].items
              })
            })
        }

        // Collection conditions need to be handled by collection endpoint instead of product endpoint
        $scope.conditions.forEach(condition => {
          if (condition.type === 'collection') {
            const collectionId = offer.condition.collection.collection.id
            Restangular.one('collections', collectionId)
              .get({ limit: 5 })
              .then(response => {
                const product_details = response.items
                product_details.item_count = response.item_count
                condition.product_details = product_details
              })
          }
        })

        // Log the offer details view for analytics
        analytics.logOfferDetailsView($scope.offer, 'Offer Details')

        // The offer progress data is not present in the offer reference that is passed to this controller, so we need to get it from the getOfferData function.
        offersService.getOfferData(offer.id, false).then(offerProgressData => {
          $scope.offerProgressData = offerProgressData
        })
        // We need to listen for the cart being synced so that the offer progress indicator
        //  will be updated when the cart changes.
        const deregisterListener = $scope.$watch(
          () => cartService.lastUpdated,
          () => {
            offersService.getOfferData(offer.id).then(offerProgressData => {
              $scope.offerProgressData = offerProgressData
            })
          }
        )
        $scope.$on('$destroy', () => {
          deregisterListener()
        })

        $scope.unlockContinuityOfferReward = offerID => {
          // Don't trigger the default offer cell modal opening
          /* eslint-disable no-restricted-globals */
          if (event) {
            event.stopPropagation()
          }
          /* eslint-enable no-restricted-globals */

          offersService.getOfferData(offerID).then(offer => {
            offerDetailsModalService.open(offer.offer)
          })
        }
      }
    }
  )
