import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('FooterControllerModule', [])
  .controller(
    'FooterController',
    function (
      $rootScope,
      $scope,
      $state,
      auth,
      config,
      eComLightService,
      headerFooterSyncService,
      lang,
      utilService
    ) {
      $scope.config = config
      $scope.utilService = utilService
      $scope.state = $state
      $scope.auth = auth
      $scope.headerFooterSyncService = headerFooterSyncService
      $scope.footerPrimaryLinks = []
      $scope.footerSyncError = false
      $scope.currentYear = new Date().getFullYear()
      $scope.afterFooterLoaded = headerFooterSyncService.afterFooterLoaded
      $scope.useAngularFooter = !config.react?.enableFooter && !$rootScope.isReactDemo()

      if (!config.disableFooterLinks.waysToShop && !eComLightService.inEComLightMode) {
        $scope.footerPrimaryLinks = $scope.footerPrimaryLinks.concat({
          label: lang.values.waysToShop,
          sref: 'main.shop',
        })
      }
      if (!config.disableFooterLinks.weeklyAd && !eComLightService.inEComLightMode) {
        $scope.footerPrimaryLinks = $scope.footerPrimaryLinks.concat({
          label: lang.values.weeklyAd,
          sref: 'main.shop.flyer',
        })
      }
      if (!config.disableFooterLinks.orderHistory) {
        $scope.footerPrimaryLinks = $scope.footerPrimaryLinks.concat({
          sref: 'main.account.orderHistory',
          label: lang.values.orderHistory,
          loggedIn: true,
        })
      }
      if (!(config.disableFooterLinks.saveForLaterLists || config.disableSaveForLaterLists)) {
        $scope.footerPrimaryLinks = $scope.footerPrimaryLinks.concat({
          sref: 'lists',
          label: lang.values.saveForLaterLists,
          loggedIn: true,
        })
      }

      // Insert custom items
      _.each(config.customFooterPrimaryLinks, link => {
        $scope.footerPrimaryLinks.splice(link.index, 0, link)
      })

      // Add `id` tag labels to primary and secondary footer links that have an `sref`
      $scope.footerPrimaryLinks = _.map(
        $scope.footerPrimaryLinks,
        utilService.generateAddIdToSrefLinkWithPrefix('footer-primary')
      )
      $scope.footerSecondaryLinks = _.map(
        config.footerSecondaryLinks,
        utilService.generateAddIdToSrefLinkWithPrefix('footer-secondary')
      )

      // Call template for footer sync and when using angular header instead of react
      if (config.dotComModule.enableHeaderFooterSync && $scope.useAngularFooter) {
        $scope.headerFooterSyncService
          .fetchFooter()
          .then(html => {
            $scope.dotComFooter = html
            $scope.footerSyncError = false
          })
          .catch(() => {
            $scope.footerSyncError = true
          })
      }
    }
  )
