// For use in the .com header/footer sync to compile the Angular components
import angular from 'angular'
import { windowManager } from '@instacart/enterprise-services-cordova'
import { renderTemplate } from '@instacart/enterprise-services-shadow-templates'

export default angular
  .module('bindHtmlCompileComponentModule', [])
  .directive('bindHtmlCompile', $compile => {
    return {
      restrict: 'A',
      scope: {
        afterCompileHandler: '=',
        bindHtmlCompile: '=',
      },
      link(scope, element) {
        scope.$watch('bindHtmlCompile', value => {
          if (!value) return

          /**
           * Bind html compile is used to render and bootstrap raw html.
           *
           * Templates make better containers for raw html than raw html.
           * They can contain scripts and styles that won't be evaluated
           * making them ideal for passing around. We can also encapsulate
           * the contents of a template onto a shadow dom.
           *
           * If the raw html just so happens to be a template
           * we will render it before bootstraping.
           *
           * The code bellow parses the raw html and trys to grab the
           * template if its the outermost tag.
           *
           * DomParser behaves different on different browsers when parsing
           * partial html.
           * On IE template tags are placed into the body tag of the Document.
           */
          const templateHtml = new DOMParser()
            .parseFromString(value, 'text/html')
            .querySelector('head > template, body > template')

          let templateRoot
          if (templateHtml) {
            const doc = document.createElement('div')
            element.append(doc)
            templateRoot = renderTemplate(doc, templateHtml)
          } else {
            element.html(value)
          }

          $compile(element.contents())(scope)
          if (scope.afterCompileHandler) {
            scope.afterCompileHandler(templateRoot || element[0])
          }

          // windowManager.listen() should have been called on app bootstrap to set up global listeners for events (clicks, window opens, comms, etc)
          // so that the Cordova container has access to them. We call it again here as we have inserted a shadowDOM root node above and the Cordova container
          // needs access to events  in teh shadow DOM as well.
          windowManager.listen()
        })
      },
    }
  })
