import React from 'react'
import PropTypes from 'prop-types'
import { withStyles, Button, LoadingDots, utils } from '@instacart/cocktail'
import CreditCardList from '../components/CreditCardList'
import PaymentIframe from '../components/PaymentIframe'
import { CARD_PROPS } from '../common/props'

const styling = tokens => {
  return {
    container: {
      position: 'relative',
      width: '100%',
      minHeight: 240,
    },
    containerLoading: {
      display: 'none',
    },
    loading: {
      margin: utils.toPx`${tokens.size.layout.medium} auto`,
      width: 0,
    },
    addCard: {
      fontWeight: tokens.font.weight.bold,
    },
  }
}

const defaultLang = {
  addCard: 'Add New Card',
}

class PaymentFormVantiv extends React.PureComponent {
  static propTypes = {
    /** List of cards */
    cards: PropTypes.arrayOf(CARD_PROPS).isRequired,
    /** Id for this component - required */
    id: PropTypes.string.isRequired,
    /** Language object for overrides */
    // eslint-disable-next-line react/forbid-prop-types
    i18n: PropTypes.object,
    /** Whether to show the new card iframe or not */
    isAddNewCard: PropTypes.bool.isRequired,
    /** Whether to show the loader or not */
    isIframeLoading: PropTypes.bool.isRequired,
    /** When a card the add new card button is clicked: onAddNewCard() */
    onAddNewCard: PropTypes.func.isRequired,
    /** The Vantiv Url to load (can be left blank not to load the iframe) */
    vantivUrl: PropTypes.string,
    /** Style overrides */
    // eslint-disable-next-line react/forbid-prop-types
    styles: PropTypes.object,
  }

  render() {
    const {
      cards,
      id,
      isAddNewCard,
      isIframeLoading,
      i18n,
      styles,
      vantivUrl,
      onAddNewCard,
      ...restProps
    } = this.props

    const lang = { ...defaultLang, ...i18n }

    return (
      <div id={id} data-test="paymentFormVantiv">
        <span css={[styles.container, isIframeLoading && styles.containerLoading]}>
          {!isAddNewCard && !isIframeLoading && (
            <>
              <CreditCardList cards={cards} id={`${id}_creditCardList`} {...restProps} />
              <Button variant="link" onClick={onAddNewCard} css={styles.addCard}>
                {lang.addCard}
              </Button>
            </>
          )}
          {/*
           * The iframe needs to be available even when hidden so that the existing Vantiv logic in
           * Angular JS is able to hook straight to the iframe as soon as this UI is rendered
           */}
          <PaymentIframe
            id="iframeVantiv"
            showIframe={isAddNewCard}
            url={vantivUrl}
            {...restProps}
          />
        </span>
        {isIframeLoading && (
          <div css={styles.loading}>
            <LoadingDots />
          </div>
        )}
      </div>
    )
  }
}

export default withStyles(styling, { displayName: 'PaymentFormVantiv' })(PaymentFormVantiv)
