import angular from 'angular'
import template from './cellSlider.html'

export default angular
  .module('CellSliderModule', [])
  .directive('cellSlider', function () {
    return {
      scope: {
        items: '=',
        location: '@',
      },
      replace: true,
      template,
      link(scope, element, _attrs) {
        let slider

        // We need this function as `.cell-slider-content` does not exist at link time
        function getSlider() {
          return slider || (slider = element[0].querySelector('.cell-slider-content'))
        }

        function updatePosition() {
          const slider = getSlider()
          scope.hasLess = slider.scrollLeft > 0
          scope.hasMore = slider.scrollLeft < slider.scrollWidth - slider.clientWidth
        }

        scope.next = function () {
          const slider = getSlider()
          slider.scrollLeft += slider.clientWidth
          updatePosition()
        }

        scope.prev = function () {
          const slider = getSlider()
          slider.scrollLeft -= slider.clientWidth
          updatePosition()
        }

        // update position after children have had a chance to initialize
        setTimeout(scope.$apply.bind(scope, updatePosition), 0)
      },
    }
  })
  .run($templateCache => {
    $templateCache.put('cellContainer.html', require('../cellContainer/cellContainer.html'))
  })
