import _ from 'lodash'
import angular from 'angular'
import template from './notificationSettingsTable.html'

export default angular
  .module('NotificationSettingsTableComponentModule', [])
  .directive('notificationSettingsTable', function (Restangular) {
    return {
      scope: {
        showGroupTitles: '=',
        atRegistration: '=',
        onSave: '=',
      },
      template,
      controller($scope, $q, $sce, config, lang) {
        $scope.config = config
        $scope.lang = lang
        $scope.payload = {}

        Restangular.one('user')
          .one('notifications')
          .get()
          .then(notifications => {
            $scope.notifications = notifications.plain()

            $scope.notificationGroups = config.notificationLabels
            _.each($scope.notificationGroups, group => {
              // only show notifications for keys that have been returned by the GET /user/notifications object
              _.remove(group.notifications, notification => {
                if (_.has($scope.notifications, notification.key)) {
                  // Map the notification into a payload for easy saving
                  $scope.payload[notification.key] = $scope.notifications[notification.key]

                  // trust more text as html as it is defined in configuration and can be trusted.
                  // trustAsHtml will convert the string to an object so check we do not convert twice
                  if (notification.moreText && typeof notification.moreText === 'string') {
                    notification.moreText = $sce.trustAsHtml(notification.moreText)
                  }

                  return false
                }
                return true
              })
              // default the set all option
              $scope.setSubToAllGroup(group)
            })
            $scope.filteredNotificationGroups = _.filter(
              $scope.notificationGroups,
              $scope.filterGroupRegistrationOnly
            )
          })

        $scope.setSubToAllGroup = function (group) {
          group.all = _.filter(group.notifications, notification => {
            return notification.showAtNotifications !== false
          })
            .map(notification => {
              return $scope.payload[notification.key]
            })
            .every(Boolean)
        }

        $scope.subToAll = function (group) {
          _.each(group.notifications, notification => {
            if (!isResponsysUnavailable(notification)) {
              $scope.payload[notification.key] = group.all
            }
          })
        }

        $scope.filterRegistrationOnly = function (notification) {
          return $scope.atRegistration
            ? notification.showAtRegistration
            : notification.showAtNotifications !== false
        }

        $scope.filterGroupRegistrationOnly = function (notificationGroup) {
          return (
            !$scope.atRegistration ||
            _.some(notificationGroup.notifications, notification => {
              return notification.showAtRegistration
            })
          )
        }
        const isResponsys = notification => notification.key.indexOf('responsys') !== -1

        const isResponsysUnavailable = notification =>
          $scope.payload[notification.key].toString().toLowerCase().indexOf('none') !== -1

        $scope.checkDisableNotification = notification =>
          isResponsys(notification) && isResponsysUnavailable(notification)
      },
      link(scope) {
        scope.onSave = function () {
          return Restangular.one('user').one('notifications').customPUT(scope.payload)
        }
      },
    }
  })
