import angular from 'angular'

// Angular Product Cell
import { isReactDemo } from '@app/utils/isReactDemo'
import ngTemplate from './productCell.ng.html'
import ngTemplate01 from './productCell.variant-01.html'
import reactTemplate from './productCell.react.html'

export default angular
  .module('ProductCellComponentModule', [])
  .directive('productCell', function (config) {
    const angularTemplate = config.enableInstacartProductCell ? ngTemplate01 : ngTemplate
    const template =
      config.react.enableNewItemTile || isReactDemo() ? reactTemplate : angularTemplate

    return {
      scope: {
        product: '=',
        promotedMedia: '=',
        location: '@',
        locationValue: '<',
        index: '<',
        recommended: '@',
        productDetailsModalLink: '=',
        promotedProduct: '<',
        isExternalRoutesDisabled: '<',
      },
      replace: true,
      template,
      controller: 'ProductCellNgController',
    }
  })
