import angular from 'angular'
import template from './lightBox.html'

export default angular.module('LightBoxComponentModule', []).component('lightBox', {
  bindings: {
    imageUrl: '<',
    imageName: '<',
  },
  controller($scope, $uibModal) {
    this.openModal = () => {
      $uibModal.open({
        template,
        controller: function lightBoxModalController($scope) {
          $scope.imageUrl = $scope.$parent.$ctrl.imageUrl
          $scope.imageName = $scope.$parent.$ctrl.imageName || 'uploaded image'
        },
        keyboard: true,
        scope: $scope,
      })
    }
  },
  template: `
      <span ng-click="$ctrl.openModal()" class="light-box-file-name">{{$ctrl.imageName}}</span>
    `,
})
