import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('masterServicesModule', [])
  .service('master', function master($rootScope) {
    const service = this

    let slaveWindows = []
    const messageListeners = []

    // Registers a callback that is called when a message from another window is received
    // eslint-disable-next-line no-multi-assign
    const addMessageListener = (service.addMessageListener = function addMessageListener(callback) {
      messageListeners.push(callback)
    })

    // Receives messages from other windows
    window.addEventListener('message', event => {
      if (event.data) {
        let msg
        try {
          msg = JSON.parse(event.data)
        } catch (e) {} // ignore non-JSON messages

        // if we received a valid JSON message, notify everyone
        if (msg) {
          messageListeners.forEach(callback => {
            callback(msg, event)
          })

          $rootScope.$apply()
        }
      }
    })

    // Send a message to another window.
    // If `targetWindow` is not specified, send it to all registered slaves
    service.postMessage = function postMessage(msg, targetWindow) {
      if (!targetWindow) {
        slaveWindows.forEach(slaveWindow => {
          if (!slaveWindow.closed) {
            slaveWindow.postMessage(JSON.stringify(msg), window.location.origin || '*')
          } else {
            slaveWindows = _.without(slaveWindows, slaveWindow)
          }
        })
      } else if (!targetWindow.closed) {
        targetWindow.postMessage(JSON.stringify(msg), '*')
      }
    }

    // Listen for messages from slave windows
    addMessageListener((msg, event) => {
      if (msg.module === 'slave') {
        switch (msg.method) {
          case 'register':
            slaveWindows = _.union(slaveWindows, [event.source])
            break
        }
      }
    })
  })
