import angular from 'angular'
import { AUTH_TYPES } from '@app/angular/constants/payments'
import { captureMessage } from '@sentry/browser'
import template from './stripeForm.html'

class StripeFormController {
  constructor($timeout, lang, stripe, Restangular, auth) {
    this.lang = lang
    this.stripe = stripe
    this.$timeout = $timeout
    this.Restangular = Restangular
    this.auth = auth

    this.card = null
    this.isSubmitting = false
    this.cardStripeElLoaded = false
    this.doNotSaveCard = auth.user.opt_out_save_payment_info || false
  }

  $onInit() {
    const elements = this.stripe.elements()

    // Custom styling can be passed to options when creating an Element.
    const style = {
      base: {
        fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
        fontSmoothing: 'antialiased',
        fontSize: '16px',
      },
      invalid: {
        color: '#B03520',
        iconColor: '#B03520',
      },
    }

    this.card = elements.create('card', { style })
    this.card.on('ready', () => (this.cardStripeElLoaded = true))
    this.card.on('change', event => (this.cardErrors = event.error ? event.error.message : ''))
    this.isStripeFormInitialized = true
    this.authType = this.authType || AUTH_TYPES.CAPTURE
    this._generateStripeCustomer()
  }

  _postPaymentInstrument(token) {
    return this.Restangular.one('user').one('payment').customPOST({
      ext_id: token.id,
      service_type: this.paymentContext,
    })
  }

  // PUT cart/payment tells the BE to generate customer id for Stripe
  _generateStripeCustomer() {
    return this.Restangular.one('cart').one('payment').customPUT({
      payment_type: this.authType,
      service_type: this.paymentContext,
    })
  }

  /**
   * Saves the user credit card
   * @returns { Promise }
   */
  async saveCreditCard() {
    this.isFormSubmitting = true

    try {
      const result = await this.stripe.createToken(this.card)

      // Inform the user if there was an error.
      if (result.error) {
        this.cardErrors = result.error.message
        throw result.error
      }

      const { token } = result
      const card = await this._postPaymentInstrument(token)

      // always save the users payment info save preference sync
      // if they dont want to save the card set the status false to remove the card
      // error doesn't block adding payment method
      try {
        if (this.doNotSaveCard) {
          card.noSave = true
          await this.Restangular.one('user/payment', card.id).remove({
            service_type: this.paymentContext,
          })
        }

        // keep this sync
        this.auth.update({ opt_out_save_payment_info: this.doNotSaveCard })
      } catch (error) {
        captureMessage(`Unable to update users payment preference: ${error?.message || error}`)
      }

      this.onAfterSave({ card })
      this.card.clear()
    } catch (error) {
      this.onSaveError({ error })
    } finally {
      this.$timeout(() => (this.isFormSubmitting = false))
    }
  }

  handleFormCancelClick() {
    this.card.clear()
    this.onCancelFormClick()
  }
}

export default angular.module('StripeFormComponentModule', []).component('stripeForm', {
  template,
  bindings: {
    authType: '<',
    isLoading: '<',
    useIcTheme: '<',
    saveLabel: '@?',
    onAfterSave: '&',
    onSaveError: '&',
    saveButtonSize: '@?',
    onCancelFormClick: '&',
    enableSolidBackground: '<?',
    paymentContext: '<',
    creditCards: '<',
    showSaveCreditCardCheckbox: '<',
  },
  controller: StripeFormController,
  controllerAs: '$ctrl',
})
