import angular from 'angular'
import template from './orderDeliveryInstructionsForm.html'

const ORDER_DELIVERY_INSTRUCTIONS = 'orderDeliveryInstructions'

/**
 * This handles the Order Delivery Instructions section in checkout, which depends on IC Fulfillment
 *
 * - The source of truth for the read only values is via the cart (in different places)
 * - For the input fields, their source is controlled via a $ctrl.draft object
 * - The address comment is synced as the address changes, depending on the app state
 */
export default angular
  .module('OrderDeliveryInstructionsFormModule', [])
  .component('orderDeliveryInstructionsForm', {
    bindings: {
      editMode: '<',
      title: '@',
      contact: '<',
      onSave: '&',
      edit: '&',
      condensed: '<',
      hideChevron: '<',
      sectionOptions: '<',
      validateCart: '&',
    },
    template,
    controller: function OrderDeliveryInstructionsForm(
      $q,
      $rootScope,
      $scope,
      addressList,
      cartService,
      checkoutService,
      config,
      lang,
      utilService
    ) {
      Object.assign(this, {
        cartService,
        config,
        lang,
        utilService,
        hasErrors: false,
        isInitialized: false,
        isSaving: false,
        watcherDestroyFns: [],
        draft: { comment: null, leaveUnattended: null },
      })

      /**
       * @private
       * Helper functions
       */
      const isCommentSet = comment => typeof comment === 'string'
      const isUnattendedSet = leaveUnattended => typeof leaveUnattended === 'boolean'

      /**
       * Initialization and Destroying
       *  Synchronize the input fields on entering edit mode
       *  If "syncSelectedAddress": Synchronize the cart if address changes
       *  If never saved a comment: Synchronize the cart if address changes
       *   (And auto save that comment if auto save is on)
       */
      this.$onInit = () => {
        this.synchronize()

        this.watcherDestroyFns = [
          $scope.$watch(
            () => this.editMode,
            editMode => editMode && this.synchronize()
          ),
          $scope.$watch(() => checkoutService.deliveryAddress, this.onAddressChange),
        ]
      }
      this.$onDestroy = () => this.watcherDestroyFns.forEach(destroyer => destroyer())

      /**
       * @private
       * Triggered when edit mode is entered, syncing the edit fields with the saved fields
       */
      this.synchronize = () => {
        const comment = this.getComment()
        const leaveUnattended = this.isUnattendedDelivery()
        Object.assign(this.draft, { comment, leaveUnattended })
      }

      /**
       * @private
       * Triggered when a new address is selected (also triggered at least once by initial address)
       * This will update the comment if it is null OR if syncing of addresses is turned on.
       * Note: This determines whether to open the UI section or not initially!
       * (The two boolean parameters are controlled via configs)
       *
       * @param {object}  newAddress - The new address object
       * @returns {*}
       */
      this.onAddressChange = newAddress => {
        const { autoSaveInitially, syncSelectedAddress } = this.sectionOptions || {}
        const { comment } = cartService.getOrderDeliveryInstruction()
        const addressComment = newAddress?.instructions
        const isInitialAutoSave = autoSaveInitially && comment === null && addressComment

        if ((syncSelectedAddress || comment === null) && newAddress?.id) {
          Object.assign(this.draft, { comment: addressComment || '' })
          return this.handleSubmit().then(() => !isInitialAutoSave && this.onSave())
        }
      }

      /**
       * Get the current comment, either as set in the cart object or the delivery address
       * comment as a default
       * @returns {string | void}
       */
      this.getComment = () => {
        const { comment } = cartService.getOrderDeliveryInstruction()
        return isCommentSet(comment) ? comment : ''
      }

      /**
       * Get the current state of unattended delivery as either set in the cart object, or using a
       * propagated config value, but false if there is alcohol!
       * : config.StepsOrder[<orderDeliveryInstructions>].options.initialUnattendedDelivery
       * @returns {boolean}
       */
      this.isUnattendedDelivery = () => {
        const { leaveUnattended } = cartService.getOrderDeliveryInstruction()
        return Boolean(
          !cartService.getHasAlcohol() &&
            (isUnattendedSet(leaveUnattended)
              ? leaveUnattended
              : this.sectionOptions?.initialUnattendedDelivery)
        )
      }

      /**
       * Whether the unattended delivery checkbox should be enabled or not.
       * For modify or alcohol, it will be disabled!
       * @returns {boolean}
       */
      this.enableUnattendedDeliveryCheckbox = () => {
        return !cartService.getHasAlcohol() && !$rootScope.modifyingOrder && !this.isSaving
      }

      /** Provides dynamic UI messaging */
      this.messages = {
        comment: () => {
          const { comment } = cartService.getOrderDeliveryInstruction()
          return comment || ''
        },
        unattendedDelivery: () => {
          const { leaveUnattended } = cartService.getOrderDeliveryInstruction()
          return leaveUnattended ? lang.values.orderDeliveryInstructions.unattendedDelivery : ''
        },
        warning: () => {
          const {
            alcoholNotice,
            warningWithUnattended,
            warningWithoutUnattended,
          } = lang.values.orderDeliveryInstructions

          if (cartService.getHasAlcohol()) return alcoholNotice
          return this.isUnattendedDelivery() ? warningWithUnattended : warningWithoutUnattended
        },
      }

      /**
       * Changes the section to edit mode by calling the edit prop function
       * @returns { undefined }
       */
      this.enterEditMode = () => this.edit({ step: ORDER_DELIVERY_INSTRUCTIONS })

      /**
       * Handles the form submit
       *
       * If has changes:
       * 1. Sync the instructions to the cart object
       * 2. Save the leaveUnattended state to the checkout model (causes section close)
       * 3. Update the address comments if the config requires it
       *
       * If not changes:
       * 1. Save the leaveUnattended state to the checkout model (causes section close)
       *
       * @returns { Promise }
       */
      this.handleSubmit = () => {
        const { comment: commentDraft, leaveUnattended: leaveUnattendedDraft } = this.draft
        const { comment, leaveUnattended } = cartService.getOrderDeliveryInstruction()
        const hasChange = commentDraft !== comment || leaveUnattendedDraft !== leaveUnattended

        this.hasErrors = false
        if (hasChange) {
          return $q
            .resolve()
            .then(() => (this.isSaving = true))
            .then(() =>
              cartService.updateOrderDeliveryInstruction(commentDraft, leaveUnattendedDraft)
            )
            .then(() => this.onSave({ leaveUnattended: leaveUnattendedDraft }))
            .then(() => this.updateAddressIfRequired(commentDraft))
            .catch(error => {
              checkoutService.logCheckoutErrorAnalytics(error)
              this.hasErrors = true
              this.onSave() // Force this section open again
              console.error(error)
            })
            .finally(() => (this.isSaving = false))
        }
        this.onSave({ leaveUnattended: leaveUnattendedDraft })
        return $q.resolve()
      }

      /**
       * @private
       * Updates the current address if the "syncSelectedAddress" is switched on
       *
       * @param {string} instructions     - The new instruction
       * @return {undefined}
       */
      this.updateAddressIfRequired = instructions => {
        const { deliveryAddress } = checkoutService
        const id = deliveryAddress?.id
        if (this.sectionOptions?.syncSelectedAddress && id) {
          Object.assign(deliveryAddress, { instructions })
          addressList.saveAddress(deliveryAddress)
        }
      }
    },
  })
