import angular from 'angular'
import { imageTransform } from '@instacart/enterprise-services'
import template from './categoryCard.html'

export default angular
  .module('CategoryCardComponentControllerModule', [])
  .component('categoryCard', {
    template,
    bindings: {
      category: '<',
      tileSize: '<',
      href: '<',
      type: '<',
      hrefOpts: '<',
    },
    controller($filter, $scope, $state, imageURLService, mobileService, config) {
      const { category, hrefOpts, tileSize = 'small', href, type } = this
      const isRecipe = type === 'recipe'

      // this comes from `shopRoutes.js` in the `main.shop.categories` definition. We have to manually set this type
      // to ensure that we pass the correct $stateParams as we transition from MLPv1 to MLPv2
      const isMLP = category?.type === 'mlp_v2'

      let hrefStateParams = { category_id: category.id }
      if (isRecipe) {
        hrefStateParams = { recipe_category_id: category.id }
      } else if (isMLP) {
        hrefStateParams = { mlp_id: category.id, mlp_slug: $filter('slugify')(category.name) }
      }

      const isIdsTile = config.react?.enableIdsCategoryTile

      $scope.reactProps = {
        imagesObj: [
          {
            src: imageTransform(
              isRecipe
                ? imageURLService.getRecipeCategoryImageURL(category)
                : category.images.tile[tileSize],
              { width: isIdsTile && !mobileService.isMobile ? 350 : 300, height: 230 }
            ),
            // image dimensions for lazy loading
            height: 190,
            width: isIdsTile && !mobileService.isMobile ? 290 : 249,
            alt: '',
          },
        ],
        linkObj: {
          href: $state.href(href, hrefStateParams, hrefOpts),
        },
        titleObj: {
          iconClass: 'icon-go',
          text: category.name,
        },
      }
    },
  })
