import angular from 'angular'

export default angular
  .module('PlaceholderImageModule', [])
  .service('placeholderImageService', function placeholderImageService(utilService) {
    const service = this

    service.getImagePath = {
      nonImage: null,
      productCell: utilService.loadStaticImage('emt-product-cell-img.svg'),
      productDetails: utilService.loadStaticImage('emt-blocks-img.svg'),
    }
  })
  .component('placeholderImage', {
    bindings: {
      type: '@',
      removeOn: '<',
      showMax: '@?',
    },
    controller($interval, placeholderImageService) {
      const vm = this
      const showMaxPlaceholders = parseInt(vm.showMax, 10) || 1

      vm.placeholderSkeleton = [{}]
      vm.placeholderImage = placeholderImageService.getImagePath[vm.type]

      if (showMaxPlaceholders > 1) {
        const loadingInt = $interval(() => {
          if (vm.removeOn || vm.placeholderSkeleton.length >= showMaxPlaceholders) {
            $interval.cancel(loadingInt)
          } else {
            vm.placeholderSkeleton.push({})
          }
        }, 500)
      }
    },
    template: `
    <div ng-if="!$ctrl.removeOn" class="loading-shimmer">
      <span ng-if="::!$ctrl.placeholderImage">Loading...</span>
      <img ng-if="::$ctrl.placeholderImage" ng-src="{{::$ctrl.placeholderImage}}" ng-repeat="cell in $ctrl.placeholderSkeleton" alt="Loading Image"/>
    </div>
  `,
  })
