import angular from 'angular'

import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import RegistrationServiceModule from './registrationService.js'
import RegistrationControllerModule from './controllers/registrationController'

export default angular
  .module('RegistrationComponentModule', [
    RegistrationServiceModule.name,
    RegistrationControllerModule.name,
  ])

  .config(($stateProvider, $urlRouterProvider, configConstants, langProvider) => {
    $stateProvider
      .state('main.changePassword', {
        url: '/change-password?resetToken',
        views: {
          'content@': {
            template: require('./views/changePassword.html'),
            controller: 'ChangePasswordController',
          },
        },
        resolve: {
          loadCtrl: $ocLazyLoad => {
            return import(
              /* webpackChunkName: "changePasswordController", webpackPrefetch: true */ './controllers/changePasswordController'
            )
              .then(mod => $ocLazyLoad.load(mod.default))
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },
          resetToken($stateParams) {
            return $stateParams.resetToken
          },
          title(titleService) {
            return titleService.setTitle(langProvider.values.changePassword)
          },
        },
        onEnter($state, resetToken) {
          // TODO: should probably validate resetToken before continue
          if (configConstants.gigya && configConstants.gigya.enabled) {
            $state.go('main.changePasswordGigya')
          } else if (resetToken === undefined || resetToken === 'true') {
            $state.go('main.home')
          }
        },
      })

      .state('main.changePasswordGigya', {
        url: '/change-password-gigya?pwrt',
        resolve: {
          passwordResetToken: $stateParams => $stateParams.pwrt,
          title: titleService => titleService.setTitle(langProvider.values.changePassword),
        },
        onEnter: ($state, gigyaService, passwordResetToken) => {
          $state.go('main.home')

          if (configConstants.gigya && configConstants.gigya.enabled && passwordResetToken) {
            gigyaService.gigyaInit('reset', passwordResetToken)
          }
        },
      })

      .state('main.socialSignOn', {
        url: '/social-sign-on?redirect',
        views: {
          'content@': {
            template: require('./views/socialSignOn.html'),
            controller: 'SocialSignOnController',
          },
        },
        resolve: {
          loadCtrl: $ocLazyLoad => {
            return import(
              /* webpackChunkName: "socialSignOnController", webpackPrefetch: true */ './controllers/socialSignOnController'
            )
              .then(mod => $ocLazyLoad.load(mod.default))
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          },
          title(titleService) {
            return titleService.setTitle(langProvider.values.socialSignOn)
          },
        },
        onEnter($state, auth) {
          // Only allow trial users (non-logged in) to go to this state.
          if (auth.user.status !== 'trial') {
            $state.go('main.home')
          }
        },
      })
      .state('main.register', {
        url: '/register?redirect',
        onEnter: (
          $state,
          $stateParams,
          $timeout,
          auth,
          config,
          redirectService,
          registrationService,
          shoppingSelectorService
        ) => {
          // Pre-emptively set context in order to hide context modal in the case
          //  where registration modal is covered by context modal on arrival to /register
          if (!shoppingSelectorService.isContextIntentInValidState()) {
            shoppingSelectorService.forceContextIntentSet()
          }
          if ($stateParams && $stateParams.redirect) {
            redirectService.saveRedirectUrl($stateParams.redirect)
          }
          if (auth.user.status !== 'trial') {
            redirectService.loadRedirectUrl({ state: 'main.home' })
          } else if (
            config.eComLight?.homeRedirect &&
            auth.getContext().shopping_mode === 'catering'
          ) {
            // Catering context overrides main.home state to specific category state set by config
            // When going to /register the redirect to main.home fails as it's overridden in stateChangeStart event on rootscope
            // Thus registration modal will never be triggered in catering context. This conditional solves the issue by going
            // directly to the category state and popping up the registration modal
            $state
              .go(config.eComLight.homeRedirect.sref, config.eComLight.homeRedirect.params, {
                reload: true,
              })
              .then(() => {
                registrationService.open('register')
              })
          } else {
            // Without the $timeout this breaks if already on main.home and clicking on a non-angular `/register` link
            $timeout(() => {
              $state.go('main.home').then(() => {
                registrationService.open('register')
              })
            }, 10)
          }
        },
      })
      .state('main.login', {
        url: '/login?redirect&norender',
        onEnter: (
          $state,
          $stateParams,
          $timeout,
          auth,
          redirectService,
          registrationService,
          shoppingSelectorService
        ) => {
          if (!shoppingSelectorService.isContextIntentInValidState()) {
            shoppingSelectorService.forceContextIntentSet()
          }
          if ($stateParams && $stateParams.redirect) {
            redirectService.saveRedirectUrl($stateParams.redirect)
          }
          if (auth.user.status !== 'trial') {
            redirectService.loadRedirectUrl({ state: 'main.home' })
          } else {
            $timeout(() => {
              // norender param can be passed here to keep the loading spinner up to avoid flashing
              // the loaded content in-between landing here and redirecting to the authorization URL
              $state.go('main.home', { norender: $stateParams.norender }).then(() => {
                registrationService.open(
                  'login',
                  null,
                  null,
                  null,
                  null,
                  null,
                  LOGIN_CONTEXTS.MAIN_LOGIN_STATE
                )
              })
            }, 10)
          }
        },
      })
      .state('main.logout', {
        url: '/logout?redirect&norender',
        onEnter: (
          $state,
          $stateParams,
          $timeout,
          redirectService,
          registrationService,
          shoppingSelectorService
        ) => {
          if (!shoppingSelectorService.isContextIntentInValidState()) {
            shoppingSelectorService.forceContextIntentSet()
          }
          if ($stateParams && $stateParams.redirect) {
            redirectService.saveRedirectUrl($stateParams.redirect)
          }
          $timeout(() => {
            // norender param can be passed here to keep the loading spinner up to avoid flashing
            // the loaded content in-between landing here and redirecting to the authorization URL
            $state.go('main.home', { norender: $stateParams.norender }).then(() => {
              registrationService.open('logout')
            })
          }, 10)
        },
      })
      .state('main.forgotPassword', {
        url: '/forgot-password',
        onEnter: (
          $state,
          $stateParams,
          $timeout,
          auth,
          redirectService,
          registrationService,
          shoppingSelectorService
        ) => {
          if (!shoppingSelectorService.isContextIntentInValidState()) {
            shoppingSelectorService.forceContextIntentSet()
          }
          if ($stateParams && $stateParams.redirect) {
            redirectService.saveRedirectUrl($stateParams.redirect)
          }
          if (auth.user.status !== 'trial') {
            redirectService.loadRedirectUrl({ state: 'main.home' })
          } else {
            $timeout(() => {
              $state.go('main.home').then(() => {
                registrationService.open('forgot')
              }, 10)
            })
          }
        },
      })
  })
