import angular from 'angular'

const backButtonListener = () => {
  // This is used for disabling the back button. Adding an event listener disables the
  // default handling of the event.
  // eslint-disable-next-line no-console
  console.log(
    'Back button was pressed. Back button is currently disabled by adding an event listener'
  )
}

const addBackButtonEventListener = () => {
  // This event is only valid on Android.
  document.addEventListener('backbutton', backButtonListener)
}

const removeBackButtonEventListener = () => {
  document.removeEventListener('backbutton', backButtonListener)
}

export default angular
  .module('barcodeScannerServicesModule', [])
  .service(
    'barcodeScanner',
    function barcodeScanner(
      $document,
      $state,
      $timeout,
      Restangular,
      errorModalService,
      mobileService
    ) {
      const service = this
      const SCANNING_SUPPORTED = window.cordova && window.cordova.plugins.barcodeScanner

      $document[0].body.className += ` ${SCANNING_SUPPORTED ? '' : 'no-'}barcode-scanner`

      service.isSupported = function () {
        return SCANNING_SUPPORTED
      }

      service.scan = function (callback, errorback) {
        const pluginOptions = {
          resultDisplayDuration: 0,
        }
        const openScanner = function () {
          if (window.navigator.notification && window.navigator.notification.beep) {
            // a11y - let user know barcode scanner open with audible beep notification
            setTimeout(() => {
              // no explicit callback available for when the barcode camera opened with this cordova plugin, so we rely on a timeout to attempt to sync beep with camera open
              window.navigator.notification.beep(1)
            }, 1700)
          }
          window.cordova.plugins.barcodeScanner.scan(callback, errorback, pluginOptions)
        }

        if (SCANNING_SUPPORTED) {
          openScanner()
        } else {
          errorback('Barcode scanning is not supported on this device.')
        }
      }

      service.search = function () {
        // Pressing the backbutton on android can sometimes trigger a back event twice here.
        // This is not an issue with the gesture back. Adding an event listener disables the
        // default handling of the event. So we add it here and remove it some time after the
        // home route is transitioned to if the scanning is cancelled else we remove it immediately
        addBackButtonEventListener()
        service.scan(
          result => {
            // result contains:
            // result.text - the code
            // result.format - the type of code (UPC_A, QR, etc..)
            // result.cancelled - the user backed out and didn't scan anything (True/False)
            if (!result.cancelled) {
              removeBackButtonEventListener()
              Restangular.one('storeproduct_scans')
                .customPOST({ upc: result.text })
                .then(response => {
                  if (response.store_product && response.store_product.id) {
                    $state.go('main.product', { id: response.store_product.id })
                  } else {
                    $state.go('main.barcodeNotFound')
                  }
                })
            } else {
              $state
                .go('main.home')
                .then(() => {
                  $timeout(removeBackButtonEventListener, 250)
                })
                .catch(removeBackButtonEventListener)
            }
          },
          error => {
            removeBackButtonEventListener()

            let errorTitle = 'Scanning failed'
            let errorBody = error

            if (error === 'Illegal access') {
              errorTitle = 'Access Required'
              errorBody = `To scan barcodes, please allow ${mobileService.appName} permission to take pictures and record video.`
            }

            errorModalService.open(errorBody, 'Ok', null, errorTitle).then(() => {
              $state.go('main.home')
            })
          }
        )
      }
    }
  )
