import angular from 'angular'

export default angular
  .module('OrderAddOnComponentModule', [])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('orderAddOn', {
      url: '/order-add-on?search_term&page',
      views: {
        nav: {
          template: require('./orderAddOnNav.html'),
          controller: 'OrderAddOnNavController',
          controllerAs: '$ctrl',
        },
        content: {
          template: require('./orderAddOn.html'),
          controller: 'OrderAddOnController',
          controllerAs: '$ctrl',
        },
      },
      onEnter: $rootScope => {
        $rootScope.showOverflow = true
      },
      onExit: $rootScope => {
        $rootScope.showOverflow = false
      },
      resolve: {
        /* Load dependencies */
        $lazyLoadControllers(utilService) {
          return utilService.lazyLoadControllers({
            OrderAddOnController: () =>
              import(
                /* webpackChunkName: "orderAddOnController", webpackPrefetch: true */ './orderAddOnController'
              ),
            OrderAddOnNavController: () =>
              import(
                /* webpackChunkName: "orderAddOnNavController", webpackPrefetch: true */ './orderAddOnNavController'
              ),
          })
        },

        /* Page utilities */
        title: titleService => {
          return titleService.setTitle(langProvider.values.orderAddOn.addOnRequest)
        },
        validStateRedirect: ($state, $stateParams, $timeout) => {
          // Initialize page by checking that user has a valid `order.id`. If `can_add_items: false`,
          // Redirect to `order.id`. If `order.id` does not exist, go back to order history page
          // $timeout is required to prevent flicker due to a known quirk with ui-router: https://github.com/angular-ui/ui-router/issues/326
          $timeout(() => {
            if ($stateParams.order && $stateParams.order.id && !$stateParams.order.can_add_items) {
              $state.go('main.account.orderHistory.order', { id: $stateParams.order.id })
            } else if (!$stateParams.order || ($stateParams.order && !$stateParams.order.id)) {
              $state.go('main.account.orderHistory')
            }
          })
        },
      },
      params: {
        order: {},
      },
    })
  })
