import angular from 'angular'
import template from './recipeDetailsModal.html'
import RecipeDetailsController from './recipeDetailsController'

export default angular
  .module('RecipeDetailsModal', [RecipeDetailsController.name])

  .service(
    'recipeDetailsModal',
    function recipeDetailsModal($uibModal, legacyModalService, uibModalAriaLabelService) {
      const service = this

      service.open = recipeId => {
        legacyModalService.closeOpenModals()
        $uibModal.open({
          template,
          controller: 'RecipeDetailsController',
          controllerAs: 'recipeDetails',
          resolve: {
            ariaLabel: () => {
              uibModalAriaLabelService.setLabel('Recipe Details')
            },
            recipe($stateParams, Restangular) {
              return Restangular.one('recipes', recipeId).get()
            },
            savedRecipes(savedRecipesService) {
              return savedRecipesService.getSavedRecipes().then(savedRecipesList => {
                return savedRecipesList
              })
            },
          },
          data: {
            setPageTitleAndBreadcrumbs: false,
          },
        })
      }
    }
  )
