import _ from 'lodash'
import fp from 'lodash/fp'
import angular from 'angular'
import tagHelper from '@app/angular/reactComponents/checkbox/tagHelper'
import { getStickyHeaderHeight } from '@app/utils/getStickyHeaderHeight'
import template from './filters.html'

export default angular
  .module('FilterComponentModule', [])
  .directive('filters', function ($state, $stateParams, eComLightService, tagsService) {
    return {
      scope: {
        filterTags: '=tags',
        showSort: '=',
        subset: '@',
        brands: '=',
      },
      template,
      controller($scope, $element, $attrs, auth, config, lang, utilService) {
        $scope.auth = auth
        $scope.config = config
        $scope.stateParams = $stateParams
        $scope.state = $state
        $scope.tagsService = tagsService
        $scope.inEComLightMode = eComLightService.inEComLightMode

        $scope.fixedTags = $scope.filterTags.map(tag => {
          const fullTag = tagsService.byVal(tag.id, 'filters')
          const tagFixes = tagHelper(fullTag)
          return (tag = { ...fullTag, ...tagFixes })
        })

        // Manage filtering
        $scope.tags = {}

        if ($stateParams.tags) {
          if (_.isArray($stateParams.tags)) {
            _.each($stateParams.tags, tags => {
              $scope.tags[tags] = true
            })
          } else {
            $scope.tags[$stateParams.tags] = true
          }
        }

        $scope.sortOptions = $scope.subset
          ? fp.flow(
              fp.filter(option => _.includes(option.showIn, $scope.subset)),
              fp.sortBy(option => option.order)
            )(config.sortOptions)
          : []

        // Set sort order, using a default value if one isn't provided

        let sortValue = $stateParams.sort

        if (!sortValue) {
          const authMode = auth.user.status === 'trial' ? 'trial' : 'loggedIn'
          sortValue = config.defaultSort[authMode][$scope.subset]
        }

        const filteredValue = _.filter($scope.sortOptions, { value: sortValue })

        $scope.sort = filteredValue[0]

        $scope.sortChange = sort => {
          $state.go('.', { sort: sort.value }).then(() => {
            utilService.focusNodeWhenInDOM('.sort select')
          })
        }

        // Set Brands

        $scope.mappedBrands = _.map($scope.brands, brand => {
          return { name: brand, value: brand }
        })

        // Perform case insensitive sorting

        $scope.mappedBrands.sort((a, b) => {
          return a.name.toLowerCase().localeCompare(b.name.toLowerCase())
        })

        $scope.onCheckboxChange = e => {
          const { checked } = e.target
          const { id } = e.target

          const newTags = $scope.tags
          newTags[id] = checked

          tagsService.checkRegistrationRequired({
            activeTags: $scope.tags,
            allTags: $scope.filterTags,
            success: () => {
              const activeTags = Object.keys(newTags).filter(key => Boolean(newTags[key]))
              $state.go('.', { tags: activeTags, page: null })
            },
          })
        }

        $scope.mappedBrands.unshift({ name: lang.values.allBrands, value: null })
        $scope.brand = _.find($scope.mappedBrands, { value: $stateParams.brand_name || null })
        $scope.stickyOffset = getStickyHeaderHeight($scope.inEComLightMode)
      },
    }
  })
