/* eslint-disable max-lines */

import _ from 'lodash'
import angular from 'angular'
import { captureMessage } from '@sentry/browser'
import { auth as authService, auth, loading, utils } from '@instacart/enterprise-services'
import { AUTH_ACTIONS } from '@app/angular/constants/authActions'
import { USER_ACTIONS, CONTEXT_ACTIONS } from '@app/angular/constants/events'
import { LOGIN_CONTEXTS } from '@app/angular/constants/loginContexts'
import { OneCartActions } from '@app/services/oneCart/types'

import { HTTP_ERRORS } from '@app/angular/constants/httpErrors'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { isNativeBridgePluginAvailable } from '@app/react/mobile/plugins/nativeBridgePlugin'
import getApiPrefix from '@app/utils/getApiPrefix'
import isCapacitor from '@app/utils/isCapacitor'
import isMobileApp from '@app/utils/isMobileApp'
import { getCurrentShoppingMode } from '@app/utils/getCurrentShoppingMode'

import ExternalOAuth2 from '@app/services/externalAuth/externalOAuth2'
import { USER_STATUSES } from '@app/angular/constants/user'

const apiPrefix = getApiPrefix()

export default angular
  .module('authServicesModule', [])
  .service(
    'auth',
    function (
      $document,
      $http,
      $injector,
      $location,
      $q,
      $rootScope,
      $state,
      $window,
      addressList,
      anonymousIdService,
      config,
      errorModalService,
      lang,
      localStorageService,
      mobileService,
      ngPromise,
      oneCartService,
      storesService,
      unataCommService,
      userService
    ) {
      const scope = this

      scope.getAuthService = function () {
        if (config.externalOAuth2?.enabled) {
          return new ExternalOAuth2()
        }
        if (config.auth?.serviceName) {
          // Load the service specified in the config
          return $injector.get(config.auth.serviceName)
        }
        if (config.gigya?.enabled) {
          return $injector.get('gigyaService')
        }
      }

      loading.store.subscribe(() => {
        // TODO: Handle 401's generically, including specific IDP auth codes
        const unauthorizedErrors = loading.selectors.getErrorsByProperty('error', 40102)
        const tokenExpiryErrors = loading.selectors.getErrorsByProperty('error', 40103)

        // Show error modal if any `40102` unauthorized errors have been thrown
        if (unauthorizedErrors.length) {
          errorModalService
            .open(
              unauthorizedErrors[0].error.message || config.lang.defaultErrorMessage,
              'OK',
              null
            )
            .then(() => {
              scope.logout()
            })
        } else if (tokenExpiryErrors.length) {
          // Show error modal if any `40103` refresh token expiry errors have been thrown
          errorModalService.open(config.lang.defaultErrorMessage, 'OK', null).then(() => {
            scope.logout()
          })
        }
      })

      auth.store.subscribe(() => {
        const user = auth.selectors.getUser()

        // Check if the user object has changed
        const hasUserChanged = !angular.equals(user, scope.user)

        // Check if the context of the user object has changed
        if (user && user.context && scope.user && scope.user.context) {
          const hasContextChanged = !angular.equals(
            _.omit(user.context, ['session_token']),
            _.omit(scope.user.context, ['session_token'])
          )
          if (hasContextChanged) {
            $rootScope.$broadcast('auth', CONTEXT_ACTIONS.CONTEXT_UPDATED)
          }
        }

        /**
         * When we log out, the auth service sets `auth.user` to `null`.
         *
         * However, when we broadcast a `null` user to the app,
         * a bunch of errors are thrown as the app always expects a user to be present.
         *
         * We work around this by only broadcasting the `userUpdated` event when the user is not `null`.
         *
         * This works fine, as the app is reloaded soon after being logged out.
         */
        if (user && hasUserChanged) {
          // Update the scoped user
          scope.user = user

          if (scope.user.context && scope.user.context.intent === 'instore') {
            body.addClass('list-mode')
          } else {
            body.removeClass('list-mode')
          }

          if (scope.user.status === 'trial') {
            localStorageService.deprecatedSet(
              'trialCartIntent',
              scope.getCurrentShoppingMode() || scope.user.context.intent
            )
          }

          $rootScope.$broadcast('auth', USER_ACTIONS.USER_UPDATED, scope.user)

          unataCommService.send({
            eventType: unataCommService.EventType.USERPROFILE,
            payload: {
              first_name: scope.user.first_name,
              last_name: scope.user.last_name,
              address: scope.user.delivery_address,
              phone: scope.user[scope.user.primary_number_key],
              email: scope.user.email,
            },
          })
        }
      })

      const body = angular.element($document[0].body)

      // This is required because of circular dependencies
      let registrationService
      const getRegistrationService = () => {
        return registrationService || $injector.get('registrationService')
      }
      scope.onlyCateringEnabled = () => {
        return (
          config.shoppingSelector.catering.exists &&
          !config.shoppingSelector.delivery.exists &&
          !config.shoppingSelector.pickup.exists &&
          !config.shoppingSelector.instore.exists
        )
      }

      scope.setContext = function (fields) {
        const finalFields = { ...fields }

        // Update shopping_mode if intent is set
        if (finalFields.intent && !finalFields.shopping_mode) {
          if (scope.onlyCateringEnabled()) {
            finalFields.shopping_mode = SHOPPING_MODES.CATERING
          } else {
            finalFields.shopping_mode = finalFields.intent
          }
        }

        if (finalFields?.intent === SHOPPING_MODES.CATERING) {
          finalFields.intent =
            config.shoppingSelector[finalFields.intent]?.behaveAs || SHOPPING_MODES.PICKUP
        }

        // Eager update context
        auth.actions.updateUserLocal({ context: finalFields })

        return ngPromise
          .wrapper(auth.promiseActions.updateUserContext)(finalFields)
          .then(() =>
            $rootScope.$broadcast('auth', CONTEXT_ACTIONS.CONTEXT_UPDATED, auth.selectors.getUser())
          )
      }

      /**
       * Updates the user context *intent* (*shopping_mode* is also updated in setContext)
       *
       * @returns {Promise}
       */
      scope.setIntent = intent => {
        return scope.setContext({ intent })
      }

      scope.getContext = auth.selectors.getUserContext

      scope.getParamsFromAddress = (address, postalCodeKey) => {
        // Context uses `deliveryPostalCode`, everything else uses `delivery_postal_code`
        return config.enableMultipleDeliveryAddresses ||
          config.shoppingContextProcess.delivery.requireFullAddress
          ? {
              address_id: address.id || '',
              // Some address objects have deliveryPostalCode instead of postal_code
              [postalCodeKey]: address.postal_code || address.deliveryPostalCode || '',
              city: address.city || '',
              province: address.province || '',
              province_short: address.province_short || '',
              // Some address objects have street_address instead of address1
              street_address: address.address1 || address.street_address || '',
            }
          : {
              address_id: '',
              // Some address objects have deliveryPostalCode instead of postal_code
              [postalCodeKey]: address.postal_code || address.deliveryPostalCode || '',
              city: '',
              province: '',
              street_address: '',
            }
      }

      scope.getContextFromAddress = address =>
        scope.getParamsFromAddress(address, 'deliveryPostalCode')
      scope.getUrlParamsFromAddress = address =>
        scope.getParamsFromAddress(address, 'delivery_postal_code')

      scope.isDeliveryContextInvalid = () => {
        const context = scope.getContext()
        // Return true if delivery mode and postal code is missing or multiple addresses and no address_id
        return (
          context.intent === 'delivery' &&
          (!context.deliveryPostalCode ||
            (config.enableMultipleDeliveryAddresses &&
              !(context.address_id && addressList.getAddressFromId(context.address_id))))
        )
      }

      scope.getAddressFromContext = auth.selectors.getAddressFromContext

      // Skip Address Entry page if gigya profile is enabled AND
      // context is pickup or context is delivery and multiple addresses are disabled.
      scope.shouldUseGigyaAddressEdit = () =>
        config.gigya.enabled &&
        config.gigya.enableProfileEdit &&
        (scope.user.context.intent === 'pickup' || !config.enableMultipleDeliveryAddresses)

      // This will give you the previous context if the context has been updated otherwise, will return null.

      // eslint-disable-next-line no-multi-assign
      const iFns = (scope.intent = {
        toString: () => scope.getContext().intent,
        pickup: () => iFns.toString() === 'pickup',
        delivery: () => iFns.toString() === 'delivery',
        inStore: () => iFns.toString() === 'instore',
      })

      scope.getStore = auth.selectors.getStore
      scope.userStoreSupportsPickup = auth.selectors.userStoreSupportsPickup
      scope.userStoreSupportsDelivery = auth.selectors.userStoreSupportsDelivery
      scope.getSessionId = auth.selectors.getSessionId
      scope.updateUser = auth.promiseActions.updateUserLocal

      /**
       * Returns the user current shopping mode, including *catering*
       * @returns {string}
       */
      scope.getCurrentShoppingMode = () => getCurrentShoppingMode(scope.getContext(), config)

      // Validate an address for the chosen fulfillment type

      scope.validateFulfillmentContactInfo = function (fulfillmentType, contactInfo) {
        if (!contactInfo.delivery_address) {
          return false
        }
        if (fulfillmentType === 'delivery') {
          return (
            contactInfo.delivery_address.first_name &&
            contactInfo.delivery_address.last_name &&
            contactInfo.delivery_address.address1 &&
            contactInfo.delivery_address.city &&
            contactInfo.delivery_address.province &&
            contactInfo.delivery_address.postal_code &&
            contactInfo.primary_number_key &&
            contactInfo[contactInfo.primary_number_key]
          )
        }
        if (fulfillmentType === 'pickup') {
          return (
            contactInfo.delivery_address.first_name &&
            contactInfo.delivery_address.last_name &&
            contactInfo.primary_number_key &&
            contactInfo[contactInfo.primary_number_key] &&
            _.find(config.deliveryAddressPhoneNumberFields, field => {
              return field.key === contactInfo.primary_number_key
            })
          )
        }

        return false
      }

      // Creates a new user session
      const createSession = () => {
        const userSession = $q.defer()

        auth.promiseActions
          .createUserSession({
            // eslint-disable-next-line no-useless-concat
            binary: `${mobileService.isMobile ? 'mobile' : 'web'}-` + `ecom`,
            binary_version: config.version,
            is_retina: false,
            os_version: navigator.platform,
            pixel_density: window.devicePixelRatio.toPrecision(2),
            push_token: '',
            // eslint-disable-next-line no-restricted-globals
            screen_height: screen.height,
            // eslint-disable-next-line no-restricted-globals
            screen_width: screen.width,
          })
          .then(userSession.resolve)

          // Error POSTing a session
          .catch(error => {
            // Clear session token and retry creating a new user session if we get back a 401 error for the user_sessions POST
            captureMessage(
              `Error creating session. Status: "${error?.status}". Message: "${
                error?.message || error
              }"`
            )
            utils.sessionToken.getToken().then(token => {
              if (error && error.status === 401 && token) {
                // Clear the old token & try creating a new session
                utils.sessionToken.removeToken().then(() => {
                  createSession()
                    // If the session is created succesfully after clearing the token, resolve
                    .then(userSession.resolve)
                    // Otherwise, reject
                    .catch(userSession.reject)
                })
              } else {
                // If the session still fails after clearing the token (401 + !sessionToken.token), or any other error is caught, reject
                const message = `Error creating user session: ${error}`
                console.error(message)
                userSession.reject(message)
              }
            })
          })

        return userSession.promise
      }

      const createTrialUser = (payload = {}) =>
        ngPromise
          .wrapper(auth.promiseActions.createTrialUser)(payload)
          .catch(error => {
            const message = `Error creating new trial user: ${error}`
            console.error(message)
            throw message
          })

      // `scope.initializeUser` flow:
      // 1. Create a user session (if that fails, retry with a new session token)
      // 2. Fetch the user
      //    a) If existing logged-in or trial user exists, update user with the context/store/zip overrides provided
      //    b) If no existing user exists, create a new user with the context/store/zip overrides provided
      // On resolve, this method promises to return a user that is ready to be used by the rest of the application
      scope.initializeUser = getUserOverridePayload => {
        const initializedUser = $q.defer()

        createSession()
          // User session created
          .then(() => {
            scope
              .fetch()

              // Existing user found, return it
              .then(initializedUser.resolve)

              // No existing user found
              .catch(error => {
                if (getUserOverridePayload) {
                  // Generate override data to use while creating new trial user (will return empty payload if no overrides)
                  getUserOverridePayload()
                    // Create the new trial user
                    .then(createTrialUser)
                    .then(initializedUser.resolve)
                    .catch(error2 => {
                      const message = `Error creating user with overrides provided in URL: "${error}", "${error2}"`
                      console.error(message)

                      initializedUser.reject(message)
                    })
                } else {
                  // Create the new trial user
                  createTrialUser().then(initializedUser.resolve).catch(initializedUser.reject)
                }
              })
          })

          // User session could not be created
          .catch(initializedUser.reject)

        // Send event that the user is logged in, either as a new or returning trialUser, or as a returning logged-in user
        return initializedUser.promise.then(scope.sendLoggedInMessage).catch(error => {
          captureMessage(`Unable to create user session: ${error?.message || error}`)
          errorModalService
            .open(
              'Your session could not be created, please reload and try again',
              lang.values.reload,
              null,
              'Invalid Session'
            )
            .then(() => {
              window.location.reload()
            })
        })
      }

      // Initialize a user or session.

      scope.init = function () {
        if (!scope.user) {
          return scope.initializeUser()
        }
        return scope.fetch()
      }

      scope.getProfile = ngPromise.wrapper(auth.promiseActions.getUserProfile)

      // Log in a user
      scope.isLoggingInDuringCheckout = false
      scope.login = function (identifier, password, socialAccount, loginContext, recaptcha) {
        // Record the trial user information before dispatching Login action because
        // auth.store.subscribe will proactively update scope.user
        const trialUserStore = scope.user.store
        const trialUserIntent = scope.user.context.intent
        const trialUserShoppingMode = scope.user.context.shopping_mode
        // Set isLoggingInDuringCheckout flag so we don't update the user's cart to a wrong state
        // during checkout due to USER_UPDATED events broadcasted to cart service. Instead, fetch
        // the cart after context and store_id is settled and based on USER_LOGGED_IN event
        if ($state.includes('main.checkout')) {
          scope.isLoggingInDuringCheckout = true
        }
        return auth.promiseActions
          .login(identifier, password, socialAccount, recaptcha)
          .then(
            // Handle a successful login
            () =>
              scope.handlePostLogin(
                !!socialAccount,
                loginContext,
                trialUserStore,
                trialUserIntent,
                trialUserShoppingMode
              ),
            // Handle a login error
            error => {
              let errorMessage = config.lang.defaultErrorMessage

              if (error) {
                errorMessage = lang.getErrorMessage(error.error) || error.message
              }

              return $q.reject(errorMessage)
            }
          )
          .finally(() => {
            scope.isLoggingInDuringCheckout = false
          })
      }

      scope.handlePostLogin = (
        socialAccount,
        loginContext,
        trialUserStore,
        trialUserIntent,
        trialUserShoppingMode
      ) => {
        return auth.promiseActions
          .getUser()
          .then(user => {
            // If logged in user does not have context intent then either use the non-logged in intent or the default.
            if (_.isNil(user.context.intent) && !$state.includes('main.checkout')) {
              const contextIntent = trialUserIntent || config.defaultShoppingContext

              return scope.setContext({ intent: contextIntent })
            }
            if ($state.includes('main.checkout')) {
              // When a user is in checkout section, once they are logged in, we do
              // not want their context to change or their selected store.
              // Additionally, we want to maintain their trial users cart.
              // Just sending the context will set the store to the default... so
              // don't do an unnecessary store update if the trialUserStore is default.
              // Only works if a config defaultStoreID is set.
              if (trialUserStore.id !== config.defaultStoreID) {
                return scope
                  .update({ store_id: trialUserStore.id })
                  .then(updatedUser => {
                    // Eagerly update store
                    auth.actions.updateUserLocal({ store: updatedUser.store })
                  })
                  .then(() => {
                    return scope.setContext({
                      intent: trialUserIntent,
                      shopping_mode: trialUserShoppingMode,
                      storeNumber: trialUserStore.id,
                    })
                  })
              }
              return scope.setContext({
                intent: trialUserIntent,
                shopping_mode: trialUserShoppingMode,
              })
            }
            scope.updateUser(user)
            if ($state.current.name === 'main.changePassword') {
              return $state.go('main.home')
            }
            return $q.resolve(user)
          })
          .then(anonymousIdService.updateBackendId)
          .then(() => {
            $rootScope.$broadcast('auth', USER_ACTIONS.USER_LOGGED_IN)
            scope.sendLoggedInMessage(scope.user)
          })
          .then(() => {
            // ReloadState only if previousState was set. Prevents loops for ry PII
            // It will only be undefined when initial state loading was interrupted
            // Additionally check that the user is not logging in from a Social Sign On link
            if ($rootScope.previousState !== undefined && !socialAccount) {
              $rootScope
                .reloadState()
                .then(() => {
                  $rootScope.$broadcast(
                    'auth',
                    USER_ACTIONS.POST_USER_LOGGED_IN_STATE_RELOAD,
                    loginContext
                  )
                })
                .catch(() => {
                  // There are cases where we transition to another state while we
                  // perform the state reload, therefore, catch it and broadcast
                  // the user has been logged in but no state reload was performed.
                  $rootScope.$broadcast(
                    'auth',
                    USER_ACTIONS.POST_USER_LOGGED_IN_NO_STATE_RELOAD,
                    loginContext
                  )
                })
            }
          })
      }

      scope.initializeContext = () => {
        return ngPromise
          .wrapper(auth.promiseActions.getUserContext)()
          .then(fetchedContext => {
            if (this.onlyCateringEnabled()) {
              fetchedContext = {
                ...fetchedContext,
                shopping_mode: SHOPPING_MODES.CATERING,
                intent: SHOPPING_MODES.PICKUP,
              }
            }
            auth.actions.updateUserLocal({ context: fetchedContext })
            $rootScope.$broadcast('auth', CONTEXT_ACTIONS.CONTEXT_UPDATED, auth.selectors.getUser())
          })
      }

      // Log out a user
      const postLogoutReload = ({ logoutFailed } = {}) => {
        if (!logoutFailed && config.singleSignOn && config.singleSignOn.enabled) {
          if (mobileService.isMobileApp()) {
            const browserTab = $window.open(
              config.singleSignOn.slsEndpoint,
              'Logout',
              'transitionstyle=crossdissolve,zoom=no'
            )
            browserTab.show()
          } else {
            $window.location.assign(config.singleSignOn.slsEndpoint)
          }
        } else if (config.returnToHomeOnLogout) {
          if (!$location.$$html5) {
            $window.location.assign($state.href('main.home'))
          } else {
            $window.history.pushState({}, '', $state.href('main.home'))
          }
          $window.location.reload()
        } else {
          $window.location.reload()
        }
      }

      const oneCartLogout = function () {
        // oneCart logout flow is a little different from the regular logout flow
        // the difference is that oneCart flow redirects to PBI as an end-result
        // documentation about this flow could be found at https://docs.google.com/document/d/1svNSjPgaUM3DL5TRu67mj3nzsQXTxgsgc4bm8ZD4SGk/edit
        // this is a WIP document.
        // Please add any new/missing functionality to this doc

        return authService.api.auth.getOneCartSignoutLink().then(response => {
          auth.promiseActions
            .logout()
            .then(() => anonymousIdService.clearId())
            .then(() => {
              const payload = {
                authenticated: false,
              }
              unataCommService.send({
                eventType: unataCommService.EventType.LOGGEDOUT,
                payload,
              })
            })
            // This will return a promise that will resolve only on cordova.
            // On non-cordova it will redirect to OneCart.
            .then(_ =>
              oneCartService.redirectToPBI(`${response.location}`, '', {
                action: OneCartActions.LOGOUT,
                method: 'GET',
              })
            )
            // Log the user out from the IAB if Native OneCart is enabled. When Native OneCart is enabled
            // the `redirectToPBI` will not log the user out from the InAppBrowser Single Sign-On session.
            .then(
              () =>
                config.oneCart.nativeOneCartEnabled &&
                isCapacitor() &&
                isNativeBridgePluginAvailable() &&
                getRegistrationService().hasOptionalAuthService() &&
                getRegistrationService().open(AUTH_ACTIONS.LOGOUT)
            )
            .then(options => !options?.skipPostLogoutReload && isMobileApp() && postLogoutReload())
            .catch(responseOrError => {
              const proceedWithReload = responseOrError?.status.toString()[0] !== '5' // Prevent infinite reloads when server goes down
              if (proceedWithReload) {
                postLogoutReload({ logoutFailed: true })
              }
            })
            .finally(() => {
              if ($window.analytics) {
                $window.analytics.reset()
              }
            })
        })
      }

      scope.logout = () => {
        // Broadcast before actually logging out so we still have user data
        $rootScope.$broadcast('auth', 'userLoggingOut')
        if (oneCartService.enabled()) return oneCartLogout()

        return auth.promiseActions
          .logout()
          .then(() => anonymousIdService.clearId())
          .then(() => {
            const payload = {
              authenticated: false,
            }

            unataCommService.send({
              eventType: unataCommService.EventType.LOGGEDOUT,
              payload,
            })
          })
          .then(
            () =>
              getRegistrationService().hasOptionalAuthService() &&
              getRegistrationService().open(AUTH_ACTIONS.LOGOUT)
          )
          .then(options => !options?.skipPostLogoutReload && postLogoutReload())
          .catch(responseOrError => {
            const proceedWithReload = responseOrError?.status.toString()[0] !== '5' // Prevent infinite reloads when server goes down
            if (proceedWithReload) {
              postLogoutReload({ logoutFailed: true })
            }
          })
          .finally(() => {
            if ($window.analytics) {
              $window.analytics.reset()
            }
          })
      }

      // Fetch updated user data from the api
      scope.fetch = ngPromise.wrapper(auth.promiseActions.getUser)

      // Update the logged in user
      scope.update = ngPromise.wrapper(auth.promiseActions.updateUser)

      // Update the logged in user's PII
      scope.updateProfile = ngPromise.wrapper(auth.promiseActions.updateUserProfile)

      scope.reloadIfUserChanged = () => {
        const currentAuth = scope.user && scope.user.status
        auth.promiseActions.getUser().then(user => {
          const newAuth = user && user.status
          if (currentAuth !== newAuth) {
            anonymousIdService
              .updateBackendId()
              .then(() => {
                $rootScope.$broadcast('auth', USER_ACTIONS.USER_LOGGED_IN)
                scope.sendLoggedInMessage(scope.user)
              })
              .then($rootScope.reloadState)
          }
        })
      }

      // Listen for a 'logout' message from unataComm to logout of a user's session
      unataCommService.listen(unataCommService.EventType.LOGOUT, () => {
        scope.logout()
      })

      // Listen for a 'authenticated-user' message from unataComm to retrieve the authenticated user
      unataCommService.listen(unataCommService.EventType.AUTHENTICATEDUSER, () => {
        scope.sendLoggedInMessage(scope.user)
      })

      // Listening for a 'set-catering-context' message from client (this is a temporary fix until deep linking is complete)
      // This will only be used from Sprouts synced footer
      unataCommService.listen(unataCommService.EventType.SETCATERINGCONTEXT, () => {
        scope.setContext({ intent: 'pickup' }).then(() => {
          $state.go('main.home', null, { reload: true })
        })
      })

      // Listening for a 'unata-available-store-contexts' message from client
      unataCommService.listen(unataCommService.EventType.GETAVAILABLESTORECONTEXTS, () => {
        scope.sendAvailableStoreContexts(scope.user)
      })

      // Listening for a 'unata-get-shopping-context' message from client
      unataCommService.listen(unataCommService.EventType.GETSHOPPINGCONTEXT, () => {
        scope.sendCurrentShoppingContext(scope.user)
      })

      // Listening for a 'unata-get-user-profile' message from client
      unataCommService.listen(unataCommService.EventType.GETUSERPROFILE, () => {
        scope.sendUserProfile()
      })

      // Determine the users primary loyaltyCard and sends a notification message regarding newly authenticated user.
      // This function is also used to provide updated user info so this event may be fired at any time.
      scope.sendLoggedInMessage = user => {
        userService.getLoyaltyCards().then(() => {
          user = user || scope.user
          if (!user) return

          const primaryCard = userService.getPrimaryLoyaltyCard()
          const payload = {
            authenticated: user.status !== 'trial',
            userId: user.id,
            firstName: user.status === 'trial' ? 'Trial User' : user.first_name || '',
            primaryLoyaltyCard: primaryCard || null,
            loyaltyCards: userService.loyaltyCards,
            store: storesService.simplifyStoreDetails(user.store),
          }

          unataCommService.send({ eventType: unataCommService.EventType.LOGGEDIN, payload })
        })

        return user
      }

      scope.sendAvailableStoreContexts = user => {
        const payload = {
          has_delivery: user.store.has_delivery && user.store.show_delivery,
          has_pickup: user.store.has_pickup && user.store.show_pickup,
        }

        unataCommService.send({
          eventType: unataCommService.EventType.AVAILABLESTORECONTEXTS,
          payload,
        })
      }

      scope.sendCurrentShoppingContext = user => {
        if (!user) return

        const payload = {
          shopping_context: user.context && user.context.intent,
        }

        unataCommService.send({
          eventType: unataCommService.EventType.SHOPPINGCONTEXT,
          payload,
        })
      }

      // Listen for the UPDATESTORE event from the unataComm service
      unataCommService.listen(unataCommService.EventType.UPDATESTORE, event => {
        storesService.fetch().then(mystores => {
          let store

          if (event.data.payload.store_id) {
            store = _.find(mystores, { id: `${event.data.payload.store_id}` })
          } else {
            store = _.find(mystores, { ext_id: `${event.data.payload.ext_id}` })
          }

          if (store) {
            // Update the users store and send an event when successful
            scope.update({ store_id: store.id }).then(() => {
              scope.sendLoggedInMessage(scope.user)
            })
          }
        })
      })

      // Sends a Unata Comm message that the user has updated their store.
      scope.sendUserUpdatedStore = user => {
        const payload = {
          store: storesService.simplifyStoreDetails(user.store),
        }

        unataCommService.send({
          eventType: unataCommService.EventType.USERUPDATEDSTORE,
          payload,
        })
      }

      // Determine the profile info for an authenticated user
      scope.sendUserProfile = () => {
        this.getProfile().then(user => {
          let payload
          if (user.status === 'trial') {
            payload = {
              error: 'This is a trial user, therefore no profile information is available',
            }
          } else {
            payload = {
              first_name: user.first_name,
              last_name: user.last_name,
              address: user.delivery_address,
              phone: user.mobile_phone_number,
              email: user.email,
            }
          }
          unataCommService.send({ eventType: unataCommService.EventType.USERPROFILE, payload })
        })
      }

      scope.getUserFullName = () => {
        if (scope.user.first_name || scope.user.last_name) {
          return [scope.user.first_name, scope.user.last_name].filter(value => !!value).join(' ')
        }

        return null
      }
    }
  )

  .run(
    (
      $rootScope,
      $state,
      Restangular,
      anonymousIdService,
      auth,
      config,
      errorModalService,
      importIcAccountService,
      lang,
      localStorageService,
      registrationService,
      userAnalytics
    ) => {
      $rootScope.$on(
        '$stateChangeError',
        (_event, toState, toParams, fromState, fromParams, error = {}) => {
          const errorData = error.data || error
          if (errorData.error === 40304) {
            // If trying to access PII after session timeout, force user to login
            registrationService.promptLogin(
              toState.name,
              toParams,
              fromState.name,
              fromParams,
              lang.values.loginToContinue
            )
          } else if (error.status === 500) {
            // Prevent loop of contant repeated requests because of server errors.
            errorModalService
              .open(errorData.message || config.lang.defaultErrorMessage, 'Reload', 'Back To Home')
              .then(() => {
                window.location.reload()
              })
          }
        }
      )

      // Prevent route changes for unregistered users on state change

      $rootScope.$on('$stateChangeStart', (event, state, params, previousState, previousParams) => {
        // Check if state requires registration and user is not registered

        // Temporary code for onecart manually handling a deeplink is not something we should be doing
        // See https://instacart.atlassian.net/browse/ENT-30964 for long term solution
        const isOneCartOrderLookup = state.name === 'main.account.orderic'
        if (isOneCartOrderLookup) {
          localStorage.setItem('icOrderId', params.id)
          localStorageService.deprecatedSet(
            'icOrderQuery',
            params.add_to_order ? { add_to_order: '' } : {}
          )
        }

        if (state.data?.requireRegistered && auth.user?.status === USER_STATUSES.TRIAL) {
          event.preventDefault() // Stop the state change for now

          // Open login / registration modal
          // setTimeout to let the event.preventDefault() change the location url back
          setTimeout(() => {
            const loginContext = isOneCartOrderLookup
              ? LOGIN_CONTEXTS.REDIRECT_ONECART_ORDER_STATUS
              : LOGIN_CONTEXTS.UNREGISTERED_ROUTE_CHANGE

            registrationService
              .open('login', null, null, null, null, params.subscription_code, loginContext)
              .then(() => {
                if (state.data.requireICAccountLink) {
                  $rootScope.$broadcast(
                    'auth',
                    'requiredLink',
                    state,
                    params,
                    previousState,
                    previousParams
                  )
                } else {
                  handleNewLogin(state, params)
                }
              })
              .catch(() => {
                // Cancelled login / registration, return home if not on a valid state
                if (!$state.current.name) {
                  $state.go('main.home')
                }
              })
              .finally(() => {
                // Remove loading indicator
                $rootScope.isAppLoading = false
              })
          }, 1)
        }
      })

      const handleNewLogin = (state, params) => {
        anonymousIdService.updateBackendId().then(() => {
          // Redirect trial users that login to the review cart page, as their cart may have changed.
          if ($state.includes('main.checkout')) {
            $state.go('main.checkout.cart')
          } else {
            // Successfully logged in / registered, continue to requested state
            $state.go(state.name, params).then(() => {
              importIcAccountService.openModal(LOGIN_CONTEXTS.UNREGISTERED_ROUTE_CHANGE)
            })
          }
        })
      }

      Restangular.setErrorInterceptor(response => {
        // Handle session expiration causing a 401 error
        if (response.status === HTTP_ERRORS.UNAUTHORIZED) {
          switch (response.data.error) {
            case HTTP_ERRORS.SESSION_EXPIRED_ERROR:
              errorModalService
                .open(
                  response.message || response.data
                    ? response.data.message
                    : null || config.lang.defaultErrorMessage,
                  'OK',
                  null
                )
                .then(() => {
                  userAnalytics.logUserForcedLogout({
                    type: 'expired_session',
                  })
                  auth.logout()
                })
              break
            case HTTP_ERRORS.REFRESH_TOKEN_ERROR:
              errorModalService.open(config.lang.expiredTokenErrorMessage, 'OK', null).then(() => {
                userAnalytics.logUserForcedLogout({
                  type: 'refresh_token',
                })
                auth.logout()
              })
              break
          }
        }
      })
    }
  )

  // Intercept HTTP requests

  .factory('JWTInterceptor', function () {
    return {
      request: cfg => {
        // Add authorization header if session token should be used
        // and is a require to our api
        if (utils.sessionToken.shouldUseToken() && cfg.url.indexOf(apiPrefix) !== -1) {
          return utils.sessionToken.getToken().then(token => {
            if (token) {
              cfg.headers.Authorization = `Bearer ${token}`
            }

            return cfg
          })
        }

        return cfg
      },
    }
  })

  .config($httpProvider => {
    $httpProvider.interceptors.push('JWTInterceptor')
  })
