import angular from 'angular'

export default angular
  .module('titleServiceComponentModule', [])
  .service('titleService', function ($q, $rootScope, config) {
    const service = this
    const configTitle = config.lang.titleTagDescription || config.lang.clientName
    const separator = ' | '

    // A promise to keep track of cases where the title is set asynchronously
    $rootScope.$on('$locationChangeSuccess', () => {
      if ($rootScope.titlePromise && $rootScope.titlePromise.promise.$$state.status > 0) {
        $rootScope.titlePromise = $q.defer()
      }
    })

    service.setTitle = function (title) {
      if (title && title.length) {
        $rootScope.title = title + separator + configTitle
      } else {
        $rootScope.title = configTitle
      }

      $rootScope.titlePromise.resolve()
      window.document.title = $rootScope.title

      return $rootScope.title
    }

    service.resetTitle = function () {
      $rootScope.title = configTitle
      $rootScope.titlePromise.resolve()
      window.document.title = $rootScope.title
    }
  })
