import angular from 'angular'
import template from '../templates/confirmationModal/confirmationModal.html'

export default angular
  .module('confirmationModalServiceModule', [])
  .service('confirmationModal', function ($uibModal, lang) {
    const service = this

    service.open = ({ title, message, confirm, cancel } = {}, templateOverride) => {
      const promise = new Promise(resolve => {
        const finalTemplate = templateOverride || template
        $uibModal.open({
          template: finalTemplate,
          controller($scope) {
            Object.assign($scope, {
              lang,
              resolve,
              title,
              message,
              confirm,
              cancel,
            })
          },
          keyboard: false,
          backdrop: 'static',
          controllerAs: '$ctrl',
        })
      })

      return promise
    }
  })
