import angular from 'angular'

import FulfillmentAddressControllerModule from './fulfillmentAddressController'
import FulfillmentTypeControllerModule from './fulfillmentTypeController'
import ReservationConfirmationControllerModule from './reservationConfirmationController'
import ReservationControllerModule from './reservationController'
import TimeslotControllerModule from './timeslotController'
import TimeslotSelectorsModule from './timeslotSelectors'
import TimeslotSelectorStateControllerModule from './timeslotSelectorState.controller'

import template from './views/timeslotSelectorState.html'

export default angular
  .module('TimeSlotComponentModule', [
    FulfillmentAddressControllerModule.name,
    FulfillmentTypeControllerModule.name,
    ReservationConfirmationControllerModule.name,
    ReservationControllerModule.name,
    TimeslotControllerModule.name,
    TimeslotSelectorsModule.name,
    TimeslotSelectorStateControllerModule.name,
  ])
  .config(($stateProvider, langProvider) => {
    $stateProvider.state('main.timeslot', {
      url: '/timeslot?redirect',
      views: {
        'content@': {
          template,
        },
      },
      resolve: {
        title(titleService) {
          return titleService.setTitle(langProvider.values.timeslot)
        },
      },
    })
  })
