import _ from 'lodash'
import angular from 'angular'
import { getSortOptions } from '@app/utils/cart'

const ITEM_GROUPING = {
  CATEGORY: 'category',
  AISLE: 'aisle',
}

export default angular
  .module('OfflineRetryInterceptorModule', [])
  .factory('OfflineRetryInterceptor', function ($injector, $q) {
    return {
      responseError(response) {
        const offline = $injector.get('offline')
        if (offline.isOfflineTriggered && !offline.isOffline) {
          const $timeout = $injector.get('$timeout')
          return $timeout(() => {
            if (offline.isOffline) {
              return $q.reject(response)
            }
            const $http = $injector.get('$http')
            return $http(response.config)
          }, offline.offlineRetryDelay)
        }
        return $q.reject(response)
      },
    }
  })
  .config([
    '$httpProvider',
    function ($httpProvider) {
      $httpProvider.interceptors.push('OfflineRetryInterceptor')
    },
  ])
  .service(
    'offline',
    function (
      $rootScope,
      $state,
      $timeout,
      config,
      localStorageService,
      mobileService,
      redirectService,
      shoppingListCache,
      userService,
      utilService
    ) {
      const service = this
      service.isOffline = false
      service.isOfflineTriggered = false
      service.offlineBuffer = config.offlineBuffer
      service.offlineRetryDelay = config.offlineRetryDelay

      // debug
      // eslint-disable-next-line no-restricted-globals
      top.offlineService = service
      service.goOnline = goOnline
      service.goOffline = goOffline

      function goOnline() {
        service.isOfflineTriggered = false
        if (!$timeout.cancel(service.currentTimeout)) {
          shoppingListCache.putCacheToCart()
          service.isOffline = false
          $rootScope.$apply()
        }
      }

      function goOffline() {
        service.isOfflineTriggered = true
        service.currentTimeout = $timeout(() => {
          redirectService.saveRedirectUrl()
          service.isOffline = true
          if ($state.current.name === 'main.inStoreList.review') {
            $state.go('offlineList')
          } else {
            $state.go('offline')
          }
          $rootScope.$apply()
        }, service.offlineBuffer)
      }

      // Check online/offline
      const addListeners = () => {
        window.addEventListener('online', goOnline)
        window.addEventListener('offline', goOffline)

        if (navigator.connection && navigator.connection.type === 'none') {
          goOffline()
        }
      }

      if (mobileService.isMobileApp()) {
        document.addEventListener('deviceready', () => addListeners())
      } else if (mobileService.isMobile) {
        addListeners()
      }

      // Watch for updates to the user object (if user is using native app, nibbler will send CORS errors otherwise)

      if (mobileService.isMobileApp()) {
        $rootScope.$on('auth', (event, action, _user) => {
          if (action === 'userUpdated') {
            userService.getLoyaltyCards().then(() => {
              const primaryCard = angular.copy(userService.getPrimaryLoyaltyCard())

              // Fetch barcode image and store in localStorage

              if (primaryCard) {
                utilService.getBase64Image(primaryCard.barcode_image).then(barcode64 => {
                  primaryCard.barcode_image = barcode64
                  localStorageService.deprecatedSet('primary-card', primaryCard)
                })
              } else {
                localStorageService.deprecatedRemove('primary-card')
              }
            })
          }
        })
      }
    }
  )

  .controller(
    'OfflineController',
    function (
      $scope,
      $state,
      $timeout,
      cartService,
      config,
      lang,
      localStorageService,
      mobileService,
      nativeMobileBrightnessService,
      offline,
      redirectService,
      shoppingListCache
    ) {
      $scope.offline = offline
      $scope.card = localStorageService.deprecatedGet('primary-card')
      $scope.lang = lang
      $scope.config = config
      $scope.$state = $state

      shoppingListCache.itemCount().then(count => {
        $scope.itemCount = count
      })

      if (mobileService.isMobileApp() && $scope.card) {
        nativeMobileBrightnessService.enableFullBrightnessMode()
        $scope.$on('$destroy', () => {
          nativeMobileBrightnessService.resetBrightnessMode()
        })
      }
      shoppingListCache.getCache().then(({ defaultHeaderImage }) => {
        $scope.defaultHeaderImage = defaultHeaderImage
      })
      $scope.getDefaultHeaderImage = () => {
        return $scope.defaultHeaderImage
      }

      $scope.tryAgain = function () {
        $scope.trying = true
        $timeout(() => {
          $scope.trying = false
        }, 5000)

        if ($state.current.name === 'offlineList') {
          shoppingListCache
            .putCacheToCart()
            .then(cartService.sync)
            .finally(() => $state.go('main.inStoreList.review'))
        } else {
          redirectService.loadRedirectUrl({ state: 'main.home' })
        }
      }

      $scope.getMessage = () => {
        if ($scope.config.offlineShoppingList.enabled && $scope.config.hasLoyalty && $scope.card) {
          return $scope.lang.values.offlineMessageLoyaltyAndList
        }
        if ($scope.config.hasLoyalty && $scope.card) {
          return $scope.lang.values.offlineMessageLoyalty
        }
        return $scope.lang.values.offlineMessage
      }
    }
  )
  .controller(
    'OfflineInStoreListReviewController',
    function OfflineInStoreListReviewController(
      config,
      configurableProductService,
      imageURLService,
      lang,
      listPrintService,
      shoppingListCache,
      sideCartSortModalService,
      utilService
    ) {
      const vm = this
      const eComExists =
        config.shoppingSelector.delivery.exist || config.shoppingSelector.pickup.exists
      const eComLightEnabled = config.eComLight.enabled
      vm.isOffline = true

      const SORT_OPTIONS = getSortOptions(lang)

      Object.assign(
        vm,
        { config, configurableProductService, imageURLService, lang, listPrintService },
        {
          hasCartItems: () => shoppingListCache.cart?.items?.length > 0,
          showOrderOnlineButton: eComExists && !eComLightEnabled,
        }
      )
      vm.cartService = shoppingListCache

      vm.model = {
        sortType: config.defaultShoppingListSort || ITEM_GROUPING.CATEGORY,
        sortedProductsBy: {},
        orderedGroups: {},
        editMode: false,
      }

      vm.setSortType = sortType => {
        vm.model.sortType = sortType
      }

      vm.newCustomItem = {
        name: '',
      }

      shoppingListCache.getCache().then(({ defaultProductImage }) => {
        vm.defaultProductImage = defaultProductImage
      })
      vm.getDefaultProductImage = () => vm.defaultProductImage

      // This is taken from the InStoreListReviewController
      // Fetch custom and non-custom items in the list
      const getListItems = () => {
        vm.model.customItems = shoppingListCache.getCustomItems()
        vm.model.nonCustomItems = shoppingListCache.getNonCustomItems()
      }

      // This is taken from the InStoreListReviewController
      // Get products sorted by Category
      const getCategorizedProducts = () => {
        shoppingListCache
          .getCategorizedProductsAndCategories()
          .then(({ productsByCategory, orderedCategories }) => {
            vm.model.customItems = shoppingListCache.getCustomItems()
            vm.model.sortedProductsBy.category = productsByCategory
            vm.model.orderedGroups.category = orderedCategories
          })
      }

      // This is taken from the InStoreListReviewController
      // Get products sorted by Aisle
      const getProductsSortedByAisle = () => {
        if (!config.disableAisles) {
          vm.model.sortedProductsBy.aisle = shoppingListCache.getAllProductsByAisle()
          vm.model.orderedGroups.aisle = _.keys(vm.model.sortedProductsBy.aisle).sort(
            utilService.naturalSortFunc
          )
        }
      }
      // This is taken from the InStoreListReviewController
      vm.model.isLoading = true
      shoppingListCache
        .fetch()
        .then(() => {
          // Prepare the default user list (through cart service)
          if (shoppingListCache.cart.items.length > 0) {
            _.each(shoppingListCache.cart.items, item => {
              if (item.store_product) {
                item.isSelected = false
              }
            })

            // Generate initial list of categorized products and custom items
            getCategorizedProducts()
            getListItems()
            getProductsSortedByAisle()

            // On cart update, re-set categorized products and custom items
            // $scope.$watch(
            //   () => cartService.lastUpdated,
            //   () => {
            //     getCategorizedProducts()
            //     getListItems()
            //     getProductsSortedByAisle()
            //   }
            // )
          }

          vm.model.isLoading = false
        })
        .catch(_err => {})

      vm.incrementItem = item => {
        item.quantity = item.quantity + 1

        shoppingListCache.updateItem({ ...item }).then(getCategorizedProducts)
      }

      vm.decrementItem = item => {
        if (item.quantity > 1) {
          item.quantity = item.quantity - 1
          shoppingListCache.updateItem({ ...item }).then(getCategorizedProducts)
          return
        }

        item.showDeleteConfirm = true
      }

      vm.updateItemQuantity = item => {
        shoppingListCache.updateItem(item).then(getCategorizedProducts)
      }

      vm.removeItem = item => {
        item.quantity = 0

        shoppingListCache.updateItem({ ...item }).then(getCategorizedProducts)
      }

      vm.removeCustomItem = itemName => {
        const customItem = shoppingListCache.cart.items.find(
          cartItem =>
            cartItem.item_type === 'pseudo_product' && cartItem.pseudo_product?.name === itemName
        )

        return this.removeItem(customItem)
      }

      vm.emptyList = () => shoppingListCache.removeItems()

      vm.updateItemComment = item => shoppingListCache.updateItem(item)

      vm.toggleAddCustom = value => {
        vm.model.showAddCustom = value
        if (vm.model.showAddCustom) {
          const customItemInput = document.querySelector('#add-custom-item-input')
          customItemInput.focus()
        } else {
          utilService.blurAndHideKeyboard()
        }
      }

      vm.addCustomItem = itemName => {
        if (itemName.length) {
          shoppingListCache
            .updateItem(
              {
                quantity: 1,
                pseudo_product: {
                  name: itemName,
                },
                item_type: 'pseudo_product',
                dirty: true,
              },
              1
            )
            .then(() => {
              vm.model.customItemInput = ''
              getListItems()
            })
        }
      }
      // I'm not sure why but there are two different versions of this function
      // in the original controller that do the same thing
      vm.addNewCustomItem = itemName => vm.addCustomItem(itemName)

      vm.setSortType = sortType => {
        vm.model.sortType = sortType
      }

      vm.sortOptionNameByValue = value => SORT_OPTIONS.find(option => option.value === value)?.name

      vm.triggerSortModal = () => {
        vm.showSortModal = true
        sideCartSortModalService
          .open({
            sortOptions: SORT_OPTIONS,
            sort: vm.model.sortType,
          })
          .then(results => {
            vm.setSortType(results.sort)
          })
          .finally(() => {
            vm.showSortModal = false
          })
      }

      // All the functions below are from the original shopping list controller
      // Theses functions edit the shopping list and should be added in to work
      // with the cache list later on.

      // Reqiures a REST api call
      vm.goToProductDetailsModal = () => shoppingListCache.notYetSupported

      vm.openShoppingSelectorModal = () => shoppingListCache.notYetSupported
    }
  )
