import angular from 'angular'
import dayjs from 'dayjs'
import { CLIENT } from '../../../rootConfig'

export default angular
  .module('debugServicesModule', [])

  .run(($rootScope, config, utilService) => {
    // Expose some debug information

    if (config.env.mode !== 'prod') {
      window.unata = {
        config,
        scopes: {
          RootScope: $rootScope,
        },
      }
    }

    utilService.log('app version:', config.version)

    // If the environment sets a hash for the version, also log the tag
    if (config.env.tag) {
      utilService.log('app tag:', config.env.tag)
    }

    // If this is using enterprise configs, log the version so you don't need to dig in the config object
    // TODO - update this with bucket config version
    if (config.$enterpriseConfigVersion) {
      utilService.log('config version:', config.$enterpriseConfigVersion)
    }

    utilService.log('build time:', dayjs(config.timestamp).format('MMM D YYYY HH:mm:ss'))

    if (config.env.mode !== 'prod') {
      utilService.log('config:', config)
    }

    if (config.env.mode !== 'prod') {
      $rootScope.$on(
        '$stateChangeError',
        (event, toState, toParams, fromState, fromParams, error) => {
          utilService.log(
            '$stateChangeError: ',
            event,
            toState,
            toParams,
            fromState,
            fromParams,
            error
          )
        }
      )
    }
    /*
     * This block introduces a new window object which can be used for debugging.
     * Although duplicated from the above, it removes the dependency on moded.
     * This is currently being used by cypress.
     *
     * TODO - refactor the above to remove the dependency on mode.
     * This would require some infra changes.
     */
    if (config.env.debug) {
      window.__UNATA_DEBUG__ = window.__UNATA_DEBUG__ || {}
      window.__UNATA_DEBUG__.config = config
      window.__UNATA_DEBUG__.CLIENT = CLIENT
    }
  })

  // Automatically generate references to all controller scopes for debugging

  .decorator('$controller', [
    '$delegate',
    'config',
    function ($delegate, config) {
      return function (constructor, locals) {
        const controller = $delegate.apply(null, arguments)

        return angular.extend(() => {
          if (config.env.mode !== 'prod' && typeof constructor === 'string' && locals.$scope) {
            window.unata.scopes[constructor] = locals.$scope
          }

          return controller()
        }, controller)
      }
    },
  ])

  // Bypath OAuth on the dev server

  .factory('OAuthInterceptor', function (configConstants) {
    const appConfig = configConstants

    return {
      request(config) {
        if (appConfig.env.mode !== 'prod') {
          config.headers['X-Unata-Dev-Auth'] = 'qel9kOMmmVQNaD8u'
        }

        return config
      },
    }
  })

  .config([
    '$httpProvider',
    function ($httpProvider) {
      $httpProvider.interceptors.push('OAuthInterceptor')
    },
  ])
