import angular from 'angular'

export default angular
  .module('InstacartCustomerLinkModalControllerModule', [])
  .controller(
    'instacartCustomerLinkModalController',
    function ($scope, skippable, startStage, linkLater) {
      $scope.skippable = skippable
      $scope.stage = startStage || ''
      $scope.linkLater = linkLater

      $scope.setStage = stage => {
        $scope.stage = stage
      }

      $scope.onDismiss = () => {
        $scope.$dismiss()
      }

      $scope.onClose = () => {
        $scope.$close()
      }
    }
  )
