import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('addToCartServicesModule', [])
  .service(
    'addToCartService',
    function addToCartService(
      $rootScope,
      analytics,
      auth,
      cartAnalytics,
      cartService,
      config,
      lang,
      mobileService,
      productsService
    ) {
      const service = this

      /*
       * Helper functions
       */
      service.getCornerDistance = (element, index) => {
        const analyticScope = cartAnalytics.findAnalyticScope(element)
        return cartAnalytics.getCornerDistanceInScope(analyticScope, index)
      }

      service.formatQuantity = (product, quantity) => {
        if (!quantity) return null

        let newQuantity = quantity

        if (product.order_by_weight) {
          newQuantity += ` ${product.ordering_parameters.display_name}`
        }

        if (!mobileService.isMobile) {
          newQuantity += ` ${lang.values.added}`
        }

        return newQuantity
      }

      service.tellParents = product => {
        $rootScope.$broadcast('productAdded', product)
      }

      // Add or update product by weight
      service.updateWeight = ({
        // Scope
        cartItem,
        element,
        product,
        index,
        isModal,
        location,
        locationValue,
        quantity,
        // Args
        configuration, // arg
        selectedQuantity, // arg
      }) => {
        // old args = configuration, selectedQuantity
        const context = auth.getContext().intent
        const updatedQuantity = selectedQuantity || quantity
        productsService.getBeacon(product.onAddToCartBeacon)

        if (!cartItem) {
          cartAnalytics.addItem(product, updatedQuantity, index, location, locationValue, {
            is_modal: isModal,
            corner_distance: service.getCornerDistance(element, index),
          })

          cartService.addItem(product, updatedQuantity, configuration)
          analytics.logFBAddToCart(product, updatedQuantity)

          if (!$rootScope.modifyingOrder) {
            analytics.logProductAddToCart(product, location, context) // GA ecom
          }

          service.tellParents(product)
        } else {
          /**
           * Please, do not move this method below *updateItemQuantity*. When selecting an item weight
           * the selected option is the final quantity - Moving it after the update method will cause
           * analytics to lose reference of the initial item quantity in the cart
           * */
          cartAnalytics.updateQuantity(product, quantity, index, location, locationValue, {
            is_modal: isModal,
            corner_distance: service.getCornerDistance(element, index),
          })

          cartService.updateItemQuantity(product, updatedQuantity, configuration)

          analytics.logEvent('add to cart weighted item update quantity', 'change')
        }
      }

      service.appReviewCheck = rateApp => {
        const context = auth.getContext().intent
        const isInstoreContent = context === 'instore'
        // if instore, and the number of items in the cart is greater than itemsAddedtoCart, then possible prompt for app review
        const cartLength = _.get(cartService, 'cart.items.length', 0)
        const itemsAdded = config.mobileAppRate.itemsAddedtoCart
        if (itemsAdded !== -1) {
          if (cartLength >= itemsAdded && isInstoreContent) {
            rateApp.promptAppReview()
          }
        }
      }

      service.registerAddToCartAnalytics = (element, index) => {
        const analyticScope = cartAnalytics.findAnalyticScope(element)

        // if there's no index meaning it's not part of a collection so no need
        // to track coordinates
        if (index !== undefined) {
          cartAnalytics.registerAddToCart(analyticScope, element)
        }
      }

      service.onAdd = ({
        element,
        index,
        isModal,
        location,
        locationValue,
        passToCtrl,
        product,
        quantity,
      }) => {
        productsService.getBeacon(product.onAddToCartBeacon)

        if (mobileService.isMobile && product.order_by_weight && passToCtrl.showPopover) {
          passToCtrl.showPopover()
        } else {
          service.tellParents()
          cartService.updateItemQuantity(product, quantity + 1)
          service.appReviewCheck()
          cartAnalytics.addItem(product, 1, index, location, locationValue, {
            is_modal: isModal,
            corner_distance: service.getCornerDistance(element, index),
          })
          analytics.logFBAddToCart(product)
          if ($rootScope.modifyingOrder) {
            analytics.logEvent(['add to cart product increment cart quantity button', 'click'])
          }
        }
      }
      service.onRemove = ({
        element,
        index,
        isModal,
        location,
        locationValue,
        product,
        quantity,
      }) => {
        if (product.order_by_weight) {
          cartAnalytics.removeItem(product, quantity, index, location, locationValue, {
            is_modal: isModal,
            corner_distance: service.getCornerDistance(element, index),
          })
          cartService.removeItem(product)
          if ($rootScope.modifyingOrder) {
            analytics.logEvent.apply(analytics.logEvent, [
              'add to cart weighted product remove from cart button',
              'click',
            ])
          }
        } else {
          cartService.updateItemQuantity(product, quantity - 1)
          cartAnalytics.removeItem(product, 1, index, location, locationValue, {
            is_modal: isModal,
            corner_distance: service.getCornerDistance(),
          })
          if ($rootScope.modifyingOrder) {
            analytics.logEvent.apply(analytics.logEvent, [
              'add to cart product decrement cart quantity button',
              'click',
            ])
          }
        }
      }
    }
  )
