import angular from 'angular'
import { MEMBERSHIP_STATUSES } from '@app/angular/constants/express'
import template from './icExpressPostCheckout.html'

const CSS_CLASS = 'ic-express-post-checkout'

class IcExpressPostCheckout {
  constructor($filter, config, express, lang, mobileService, utilService) {
    const { icExpress } = config
    const { postCheckout } = icExpress.placements

    Object.assign(this, {
      $filter,
      config,
      express,
      lang,
      mobileService,
      placementConfig: postCheckout,
      utilService,
      css: sub => CSS_CLASS + (sub ? `__${sub}` : ''),
    })
  }

  $onInit() {
    this.enableRetailerTheme = this.enableRetailerTheme || this.config.icExpress.enableRetailerTheme
    this.title =
      this.title ||
      this.lang.template(this.placementConfig.title, {
        deliveryFee: this.$filter('currency')(this.deliveryFee || 0),
      })
    this.image = this.image || this.placementConfig.image
    this.imageAlt = this.imageAlt || this.placementConfig.imageAlt
    this.imageMobile = this.imageMobile || this.placementConfig.imageMobile
    this.subtitle = this.subtitle || this.placementConfig.subtitle
    this.buttonLabels = this.buttonLabels || this.placementConfig.buttonLabels
    this.list = this.list || this.placementConfig.list
  }

  /**
   * Returns whether the user has a subscription, or null if the subscription has not been acquired yet
   *
   * @returns { null | boolean }
   */
  get hasSubscription() {
    const subscription = this.userSubscription?.subscription
    if (!subscription) return null
    return !!subscription.id
  }

  /**
   * Returns whether the user is eligible for a trial
   *
   * @returns { boolean }
   */
  get isTrialEligible() {
    return this.express.getMembershipStatus() === MEMBERSHIP_STATUSES.TRIAL_ELIGIBLE
  }
}

export default angular
  .module('IcExpressPostCheckoutComponentModule', [])
  .component('icExpressPostCheckout', {
    template,
    bindings: {
      // MANDATORY
      onClick: '&', // Function to call when user clicks sign up
      deliveryFee: '<', // The delivery fee. This will be hidden if it is 0
      isSubscribing: '<', // true if a subscription api request is currently in progress (shows loader)
      userSubscription: '<', // The current user subscription, or null if still loading!

      // OPTIONAL
      list: '<?',
      title: '@?',
      image: '@?',
      imageAlt: '@?',
      subtitle: '@?',
      imageMobile: '@?',
      buttonLabels: '<?',
      enableRetailerTheme: '<?',
    },
    controller: IcExpressPostCheckout,
  })
