import _ from 'lodash'
import angular from 'angular'
import urlParser from '@app/utils/urlParser'

const DEFAULT_SEARCH_ROUTE = 'main.search'
const ORDER_ADDON_ROUTE = 'orderAddOn'
const ORDER_ITEM_APPEND_ROUTE = 'main.account.orderHistory.order.edit.append'
const RECIPES_SEARCH_ROUTE = 'main.search.recipes'
const CONTENT_SEARCH_ROUTE = 'main.search.types'

const SEARCH_ROUTES = {
  default: DEFAULT_SEARCH_ROUTE,
  order_add_on: ORDER_ADDON_ROUTE,
  order_item_append: ORDER_ITEM_APPEND_ROUTE,
  recipes: RECIPES_SEARCH_ROUTE,
  content: CONTENT_SEARCH_ROUTE,
}

export default angular
  .module('UnataSearchNavModule', [])
  .component('unataSearchNav', {
    bindings: {
      isSearching: '<?',
      focusSearchBar: '<?', // parent may instruct to focus the search input by setting focusSearchBar to true
      focusInitially: '<?', // parent may initially focus the search input by setting focusInitially to true
      setSearchingTo: '&?',
      searchTerm: '<?',
      liteMode: '<?',
    },
    controller(
      $location,
      $q,
      $rootScope,
      $state,
      $timeout,
      $window,
      analytics,
      config,
      eComLightService,
      lang,
      mobileService,
      Restangular,
      searchService
    ) {
      const vm = this

      vm.config = config
      vm.isMobile = mobileService.isMobile
      vm.lang = lang
      vm.searchFieldType = mobileService.isMobile ? 'input' : 'search'
      vm.typeaheadWaitTime = 400
      vm.resultsVoiceOver = ''

      // TODO: Remove this <strong> removal function and its usage in vm.autocomplete once the
      //  back end stops sending us <strong> tags in the product autocomplete suggestions
      const removeStrongTags = (searchTerm = '') =>
        searchTerm && searchTerm.replace(/<strong>/g, '').replace(/<\/strong>/g, '')

      vm.autocomplete = () => {
        // Clear autocompleteResults to ensure we don't accidentally send users to old results if they execute a
        //  search before the call to `/autocomplete` has a chance to return
        vm.autocompleteResults = []

        if (!vm.transitioningToSearchResult) {
          return Restangular.oneUrl('autocomplete')
            .get({ search_term: vm.searchTerm })
            .then(results => {
              const redirectResults = results.redirects
              const productResults = results.product_autocompletes

              if (redirectResults && productResults) {
                // The back end returns a list of search redirects (any page),
                //  as well as a list of normal autocomplete search suggestions (for products).
                // Here we handle both of these lists and append them together in such a way as to
                //  display them correctly in the autocomplete dropdown (ui-typeahead)
                let convertedRedirectSuggestions = []

                if (!vm.liteMode) {
                  convertedRedirectSuggestions = _.map(
                    redirectResults,
                    (redirectSuggestion, index) => ({
                      // `label` will be accessible as `match.label` in the ui-typeahead templates
                      label: redirectSuggestion.redirect_name,
                      // the following params will be accessible inside `match.model` in the ui-typeahead templates
                      isRedirect: true,
                      isLastRedirect: index === redirectResults.length - 1,
                      redirectCategory: redirectSuggestion.redirect_category,
                      redirectIsExternal: redirectSuggestion.is_external_url,
                      redirectUrl: redirectSuggestion.url,
                    })
                  )
                }

                const convertedProductSuggestions = _.map(productResults, productSuggestion => ({
                  // `label` will be accessible as `match.label` in the ui-typeahead templates
                  label: removeStrongTags(productSuggestion),
                  // the following params will be accessible inside `match.model` in the ui-typeahead templates
                  isRedirect: false,
                }))

                const resultsAvailable = productResults.length
                const contextSuggestionLabel = removeStrongTags(productResults[0])

                const contextSuggestionRecipes = {
                  label: contextSuggestionLabel,
                  isRedirect: false,
                  context: 'recipes',
                }

                const contextSuggestionContent = {
                  label: contextSuggestionLabel,
                  isRedirect: false,
                  context: 'content',
                }

                if (convertedRedirectSuggestions.length + convertedProductSuggestions.length > 0) {
                  const contextSuggestions =
                    resultsAvailable && !vm.liteMode
                      ? [
                          ...(searchService.isRecipesEnabled() ? [contextSuggestionRecipes] : []),
                          ...(searchService.isDotComEnabled() ? [contextSuggestionContent] : []),
                        ]
                      : []

                  const newProducts = injectContextResults(
                    convertedProductSuggestions,
                    contextSuggestions
                  )

                  vm.autocompleteResults = [...convertedRedirectSuggestions, ...newProducts]
                }

                vm.resultsVoiceOver = `${vm.autocompleteResults.length} results available`

                return vm.autocompleteResults
              }
              $q.reject()
            })
            .catch(() => [])
        }
      }

      const injectContextResults = (products, contextResults) => {
        // inject the context results after the first matched product result
        const productsClone = angular.copy(products)
        productsClone.splice(1, 0, ...contextResults)
        return productsClone
      }

      const getRoutes = searchRoutes => {
        // eslint-disable-next-line no-restricted-syntax
        for (const key in searchRoutes) {
          if (searchRoutes[key] === $state.current.name) {
            return searchRoutes[key]
          }
        }
      }

      const sendToSearch = {
        page: ({ label, context, isAutocomplete }) => {
          const destinationState =
            SEARCH_ROUTES[context] || getRoutes(SEARCH_ROUTES) || DEFAULT_SEARCH_ROUTE
          return $state.go(destinationState, {
            search_term: label,
            search_is_autocomplete: isAutocomplete || null,
            tags: null,
            page: null,
            category_id:
              (eComLightService.inEComLightMode && eComLightService.eComLightCategory) || null,
            brand_name: null,
            brand_names: null,
            sort: null,
          })
        },
        redirect: ({ redirectIsExternal, redirectUrl }) => {
          if (redirectIsExternal) {
            const redirectHasProtocol = redirectUrl.indexOf('http') === 0
            // Handle the case where an external link is provided without a protocol,
            //  otherwise the user is redirected to `<current url> + redirectUrl` in the new tab
            $window.open(`${!redirectHasProtocol ? 'http://' : ''}${redirectUrl}`, '_blank')
          } else {
            // Redirect is claimed to be internal

            const isMobileApp = mobileService.isMobileApp()
            const parsedRedirectUrl = urlParser(redirectUrl)
            // Use url specified at build time if it's cordova
            const currentHostname = isMobileApp
              ? urlParser(vm.config.env.webUrl).host
              : $location.host()

            if (currentHostname && currentHostname === parsedRedirectUrl.hostname) {
              // If the redirect is indeed to an internal page, redirect to it with $location.url
              $location.url(`${parsedRedirectUrl.pathname}${parsedRedirectUrl.search}`) // Does not reload the site
            } else {
              // If the redirect is in fact to an external page (but not set as external)
              // Load the redirect in the same tab or system browser if cordova
              $window.open(redirectUrl, isMobileApp ? '_system' : '_self')
            }
          }

          return $q.resolve()
        },
      }

      /**
       * Handles when the user tries to perform a search, either by submitting
       * the search input or by selecting an option in the typeahead menu.
       * @param {Object} searchDestinationData An object containing the typeahead label, context, and whether it's a redirect
       * @param {string} source The source of the search submit being triggered
       */
      const processSearchSubmit = (searchDestinationData, source = 'navigation') => {
        const isAutocomplete = source === 'autocomplete'
        const { label, isRedirect, context } = searchDestinationData
        searchDestinationData.isAutocomplete = isAutocomplete
        const sendToSearchMethod = isRedirect ? 'redirect' : 'page'
        const search_request_id = searchService.startNewSearchSession()

        let search_source = source
        if (context) {
          search_source = `${search_source}_${context}`
        }

        analytics.logEvent('nav search', 'submit', label)
        analytics.logSegmentEvent('search.search', {
          is_autocomplete: isAutocomplete,
          search_value: label,
          search_source,
          snrid: search_request_id,
          is_search_redirect: isRedirect,
          search_redirect_data: isRedirect
            ? {
                search_redirect_name: label,
                search_redirect_category: searchDestinationData.redirectCategory,
                search_redirect_is_external: searchDestinationData.redirectIsExternal,
                search_redirect_url: searchDestinationData.redirectUrl,
              }
            : null,
        })
        sendToSearch[sendToSearchMethod](searchDestinationData).finally(() => {
          $rootScope.$broadcast('searchProcessingComplete')
          vm.transitioningToSearchResult = false
          document.activeElement.blur() // hide the keypad

          if (vm.isMobile) {
            const autoCompleteContainer = document.querySelector('.search-results-menu')
            const autoCompleteContainerExists = document.body.contains(autoCompleteContainer)
            if (autoCompleteContainerExists && vm.typeahead_popupOpen) {
              document.querySelector('#react').click()
            }
          }
        })
      }

      vm.search = ($model, $label, source) => {
        if ((!$model && vm.searchTerm.length > 0) || $model) {
          vm.transitioningToSearchResult = true // will blur the input because of ng-disabled

          // Because ui-typeahead waits 400ms before calling `vm.autocomplete` which searches for new autocomplete results (and
          //  clears the existing autocomplete results), we have to make sure the last associated call to that function was executed
          //  before determining where to send the user on search.
          // (Unfortunately the directive does not give us a way to know when it is waiting/debouncing the user's input, it only
          //  allows us to detect when it is actually autocompleting)
          setTimeout(() => {
            if (!$model) {
              // User entered in a search query and pressed enter

              const firstDropdownResult = vm.autocompleteResults && vm.autocompleteResults[0]
              if (
                config.searchNavEnableFirstRedirectOnEnter &&
                firstDropdownResult &&
                firstDropdownResult.isRedirect
              ) {
                // If config.searchNavEnableFirstRedirectOnEnter is true, and there's a redirect showing in the dropdown,
                //  send the user to that redirect
                processSearchSubmit(firstDropdownResult, source)
              } else {
                // Otherwise, send them to the search page with vm.searchTerm as the search_term param
                processSearchSubmit(
                  {
                    label: vm.searchTerm,
                    isRedirect: false,
                  },
                  source
                )
              }
            } else {
              // User explicitly selected a dropdown search result (with keyboard or mouse)
              processSearchSubmit($model, source)
            }
          }, vm.typeaheadWaitTime)
        }
      }

      vm.clearSearch = keepFocus => {
        if (vm.isMobile && vm.searchTerm && vm.searchTerm.length && !keepFocus) {
          vm.setSearchingTo({ newValue: false })
        }
        vm.searchTerm = ''
        vm.focusSearchBar = false

        if (vm.isMobile && keepFocus) {
          vm.focusSearchBar = true
          $timeout(() => {
            vm.focusSearchBar = false
          })
        }
      }

      vm.onBlur = blurEvent => {
        if (vm.isMobile) {
          const searchContainer = document.querySelector('.mobile-search-bar')
          const blurTargetIsSearch = !!(
            blurEvent &&
            blurEvent.relatedTarget &&
            searchContainer.contains &&
            searchContainer.contains(blurEvent.relatedTarget)
          )

          if (blurEvent.relatedTarget !== null && !blurTargetIsSearch) {
            // If the next target for blur event is not within the search module,
            // set isSearching to false to hide the search overlay. This would not otherwise
            // happen for keyboard users who e.g. tab away from search input.
            vm.setSearchingTo({ newValue: false })
          }
        }
        vm.isInFocus = false
      }

      vm.onFocus = () => {
        if (vm.isMobile) {
          // eslint-disable-next-line no-unused-expressions
          vm?.setSearchingTo({ newValue: true })
        }
        vm.isInFocus = true
      }
    },
    // type="search" doesn't show the 'X' to clear the input on iOS.
    // https://stackoverflow.com/questions/35583503/input-type-search-no-longer-shows-cancel-button-x-under-ios
    template: `
      <div class="search-nav" ng-class="{searching: $ctrl.isSearching, 'lite-mode': $ctrl.liteMode}">
        <form
          ng-submit="$ctrl.search(null, null, 'navigation')"
          ng-class=":: {'button-right': !$ctrl.isMobile}"
         >
          <span
            class="screen-reader-text"
            id="search-nav-input-screen-reader-description"
            aria-hidden="{{!$ctrl.isInFocus}}"
          >
            <span ng-if="$ctrl.isMobile">{{::$ctrl.lang.values.searchScreenReaderDescriptionMobile}}</span>
            <span ng-if="!$ctrl.isMobile">{{::$ctrl.lang.values.searchScreenReaderDescriptionDesktop}}</span>
          </span>
          <span>
            <input id="search-nav-input"
              data-test="search-nav-input"
              class="input"
              name="search"
              ng-attr-type="{{$ctrl.searchFieldType}}"
              ng-class=":: {'mobile-search-bar-input': $ctrl.isMobile}"
              ng-blur="$ctrl.onBlur($event)"
              ng-focus="$ctrl.onFocus()"
              ng-model="$ctrl.searchTerm"
              ng-disabled="$ctrl.transitioningToSearchResult"
              placeholder="{{::$ctrl.lang.values.searchPlaceholder}}"
              aria-label="{{::$ctrl.lang.values.searchPlaceholder}}"
              autocomplete="off"
              aria-autocomplete="list"
              aria-describedby="search-nav-input-screen-reader-description"
              aria-expanded="$ctrl.typeahead_popupOpen"
              aria-controls="typeahead-search-results-list"
              aria-haspopup="listbox"
              aria-multiline="false"
              uib-typeahead="searchResult as searchResult.label for searchResult in $ctrl.autocomplete()"
              typeahead-accessibility
              typeahead-is-open="$ctrl.typeahead_popupOpen"
              typeahead-min-length="3"
              typeahead-wait-ms="::$ctrl.typeaheadWaitTime"
              typeahead-on-select="$ctrl.search($model, $label, 'autocomplete')"
              typeahead-focus-first="false"
              typeahead-focus-on-select="false"
              typeahead-popup-template-url="typeahead-popup-template.html"
              typeahead-template-url="typeahead-match-modified-uibootstrap.html"
              search-dom-handling
              clear-search="$ctrl.clearSearch()"
              focus-when="$ctrl.focusSearchBar"
              focus-initially="$ctrl.focusInitially"
            />
          </span>

          <span class="screen-reader-text" aria-live="assertive">{{$ctrl.resultsVoiceOver}}</span>

          <span ng-if=":: !$ctrl.isMobile || $ctrl.liteMode">
            <button id="search-nav-search" class="search-control-button search-nav-search" data-test="search-nav-search" aria-label="{{::$ctrl.lang.aria.search}}">
              <span class="icon-search" aria-hidden="true"></span>
            </button>
          </span>

          <span ng-if="::$ctrl.isMobile" class="search-nav-icon-container">
            <span role="button" ng-show="!$ctrl.liteMode" class="search-control-button search-nav-clear" ng-click="$ctrl.search(null, null, 'navigation')" aria-label="Submit Search" id="search-nav-clear">
              <span class="icon-search" aria-hidden="true"></span>
            </span>
            <span role="button" ng-show="$ctrl.searchTerm" ng-click="$ctrl.clearSearch(true)" class="search-control-button search-nav-clear-delete" data-test="search-nav-clear-delete" aria-label="Clear Search" id="search-nav-clear-delete">
              <span class="icon-delete" aria-hidden="true"></span>
            </span>
            <span role="button" ng-if="::$ctrl.config.enableBarcodeScanner" id="search-nav-barcode-scanner" aria-label="Scan Barcode" barcode-scanner-button>
              <span class="icon-scanner" aria-hidden="true"></span>
            </span>
          </span>

        </form>
      </div>
    `,
  })

  .directive('searchDomHandling', function ($timeout, mobileService) {
    return {
      restrict: 'A',
      scope: {
        focusWhen: '<?',
        focusInitially: '<?',
        clearSearch: '&',
      },
      link($scope, $element, $attrs) {
        const searchInput = $element[0]
        // In mobile the parent might trigger a focus on the search input
        if (mobileService.isMobile && $attrs.focusWhen) {
          if ($scope.focusWhen) {
            $timeout(() => {
              searchInput.focus()
            })
          }

          $scope.$watch('focusWhen', (newVal, oldVal) => {
            if (newVal !== oldVal && newVal) {
              $timeout(() => searchInput.focus())
            }
          })
        }

        if ($attrs.focusInitially) {
          const unbind = $scope.$watch('focusInitially', newVal => {
            if (newVal) $timeout(() => searchInput.focus())
            unbind()
          })
        }

        // Search input needs to be blurred after search fires and cleared when new page is not search results
        $scope.$on('$stateChangeSuccess', (_event, toState) => {
          // If new page is not a search route, clear search input
          if (!Object.values(SEARCH_ROUTES).includes(toState.name)) {
            $scope.clearSearch()
          }
        })
      },
    }
  })
  .run($templateCache => {
    $templateCache.put(
      'typeahead-popup-template.html',
      require('../shared/typeahead/typeahead-popup-template.html')
    )
    $templateCache.put(
      'typeahead-match-modified-uibootstrap.html',
      require('../shared/typeahead/typeahead-match-modified-uibootstrap.html')
    )
  })
