import _ from 'lodash'

// TODO - make this more readable
export const productPlacementsFromProducts = results => {
  return results?.placements?.length > 0
    ? results.placements
        // eslint-disable-next-line array-callback-return
        .map(placement => {
          if (placement.item_type === 'product') {
            placement.product.placement = true
            return placement.product
          }
        })
        .filter(item => item)
    : []
}

// Massage the image placements returned from /store_products into an object as returned
// by /placements for consistent use within analytics
export const formatImagePlacementForAnalytics = (placement, type) => ({
  route: type,
  media: { type: 'image' },
  target: placement.target,
  name: placement.name,
  id: placement.id,
})

export const insertPlacementsInProducts = (placements = [], products = []) => {
  /* Both splice and the spread operator which are used below remove all
  non numeric keys from the products object:

  Example: products = { 0: item1, ... 24: item24, item_count: 25, placements: [] }
  Becomes: products = { 0: item1, ... 24: item24 }

  To avoid that, and retain all non numeric keys so as to mutate the object as
  as little as possible, the code below copies the item from the object, splices
  the ads into the correct locations, then manually ads the numeric keys back into
  the correct spots in the object

  NOTE: This will only insert product placements into the array, not image placements

  Input: products = { 0: item1, ... 24: item24, item_count: 25, placements: [{ id: item123, position: 1 }], ... }
  Output: products = { 0: item123, 1: item1, ... 24: item24, item_count: 26, ... }
  */

  // IC, Elevaate, or Criteo ads.
  const positionedPlacements = []
  // Unata product placements.
  const unpositionedProductPlacements = []
  let areUnpositionedProductPlacementsFirst
  // Unata image placements.
  const unpositionedNonProductPlacements = []
  placements.forEach(placement => {
    if (placement.item_type !== 'product') {
      unpositionedNonProductPlacements.push(placement)
    } else if (typeof placement.product?.position === 'number') {
      positionedPlacements.push(placement)
      areUnpositionedProductPlacementsFirst ??= false
    } else {
      unpositionedProductPlacements.push(placement)
      areUnpositionedProductPlacementsFirst ??= true
    }
  })

  // Populate the placement list only with unpositioned non-product placements.
  products.placements = unpositionedNonProductPlacements

  // Order positioned product placements by their position to insert them into
  // the product list later.
  const sortedPositionedPlacements = _.sortBy(
    positionedPlacements,
    placement => placement.product.position
  )

  // Insert positioned product placements at their respected positions.
  sortedPositionedPlacements.forEach(placement => {
    const index = placement.product.position - 1
    products.splice(index, 0, placement.product)
  })

  // Determine an index at which to insert unpositioned placements relative to positioned ones.
  let unpositionedProductPlacementsIndex = 0
  if (sortedPositionedPlacements.length) {
    const relativePositionedPlacement = areUnpositionedProductPlacementsFirst
      ? sortedPositionedPlacements[0]
      : sortedPositionedPlacements[sortedPositionedPlacements.length - 1]
    unpositionedProductPlacementsIndex = relativePositionedPlacement.position ?? 0
  }

  // Insert unpositioned placements.
  products.splice(
    unpositionedProductPlacementsIndex,
    0,
    ...unpositionedProductPlacements.map(({ product }) => product)
  )

  return products
}
