import angular from 'angular'

import OfflineNavTemplate from './offlineNav.html'
import OfflineTemplate from './offline.html'
import OfflineRetryInterceptorModule from './offline.js'

export default angular
  .module('OfflineComponentModule', [OfflineRetryInterceptorModule.name])
  .config($stateProvider => {
    $stateProvider
      .state('offline', {
        url: '/offline',
        views: {
          nav: {
            template: OfflineNavTemplate,
            controller: 'OfflineController',
          },
          content: {
            template: OfflineTemplate,
            controller: 'OfflineController',
          },
        },
      })
      .state('offlineList', {
        url: '/offline/list',
        views: {
          nav: {
            template: OfflineNavTemplate,
            controller: 'OfflineController',
          },
          content: {
            template: require('@app/angular/components/inStoreList/review/inStoreListReview.html'),
            controller: 'OfflineInStoreListReviewController',
            controllerAs: '$ctrl',
          },
        },
        resolve: {
          redirect($state, $timeout, config, offline) {
            if ($state.current.name !== 'offline' && !offline.isOffline) {
              return $timeout(() => {
                $state.go('main.home')
              })
            }
            if (!config.offlineShoppingList.enabled) {
              return $timeout(() => {
                $state.go('offline')
              })
            }
          },
        },
      })
  })
