import angular from 'angular'
import { EXPIRATION_DATE_FORMAT } from '@app/angular/constants/express'
import template from './thirdStep.html'

class ExpressCancelFirstStep {
  constructor($filter, config, express, lang) {
    Object.assign(this, {
      config,
      express,
      lang,
      currencyFilter: $filter('currency'),
    })
  }

  $onInit() {
    const { trial, membership } = this.lang.values.icExpress

    this.options = this._getOptions()
    this.plan = this.express.isTrial() ? trial : membership
  }

  cancelMembership() {
    this.onMembershipCancelClick({
      data: {
        option: this.cancelOption,
        details: this.cancelText,
      },
    })
  }

  getCancellationTitle() {
    const {
      membershipCancellationTitle,
      trialCancellationTitle,
    } = this.config.icExpress.cancellation.thirdStep

    const title = this.express.isTrial() ? trialCancellationTitle : membershipCancellationTitle

    return this.lang.template(title, {
      chargeDate: this.express.getMembershipEndDate().format(EXPIRATION_DATE_FORMAT),
    })
  }

  getCancellattionText() {
    const {
      membershipRefundText,
      membershipText,
      trialCancellationText,
    } = this.config.icExpress.cancellation.thirdStep

    return this.lang.templateAsString(
      this.express.isTrial()
        ? trialCancellationText
        : this.express.isRefundEligible()
        ? membershipRefundText
        : membershipText,
      {
        express: this.config.icExpress.shortName,
        amount: this.currencyFilter(
          this.express.userCurrentSubscription.subscription.subscription_plan.price
        ),
      }
    )
  }

  getRenewReminderText() {
    return this.lang.templateAsString(this.config.icExpress.cancellation.renewReminder, {
      plan: this._getPlanName(),
    })
  }

  getCancelButtonLabel() {
    return this.lang.templateAsString(
      this.config.icExpress.cancellation.thirdStep.cancelButtonLabel,
      { plan: this._getPlanName() }
    )
  }

  _getPlanName() {
    const { membership, trial } = this.lang.values.icExpress

    return this.express.isTrial() ? trial : membership
  }

  _getOptions() {
    const options =
      (this.express.userCurrentSubscription &&
        this.express.userCurrentSubscription.subscription &&
        this.express.userCurrentSubscription.subscription.subscription_plan &&
        this.express.userCurrentSubscription.subscription.subscription_plan.cancellation_reasons) ||
      {}

    return Object.keys(options).map(option => {
      return {
        id: option,
        label: options[option],
      }
    })
  }
}

export default angular
  .module('IcExpressCancelThirdStepComponentModule', [])
  .component('expressCancelThirdStep', {
    template,
    bindings: {
      useIcTheme: '<',
      onExitClick: '&',
      onSendReminderClick: '&',
      onMembershipCancelClick: '&',
    },
    controller: ExpressCancelFirstStep,
  })
