import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('orderWeightOptionsServiceModule', [])
  .service('orderWeightOptionsService', function orderWeightOptionsService() {
    /**
     * Memoized generator for order weight options
     * (must have one param only to work with _.memoize)
     */
    this.generate = _.memoize(
      /**
       * Generates order weight options
       *
       * @param {object} product                        - The product item
       * @param {boolean} [includeZeroOption = false]   - Whether to include a 0lbs option or not
       * @returns {{object}[]}                          - Array of label/value objects
       */
      ({ product, includeZeroOption = false }) => {
        const { order_by_weight, ordering_parameters } = product || {}

        if (order_by_weight) {
          const { display_name, increment, min, max } = ordering_parameters || {}
          const result = includeZeroOption ? [0] : []
          let ii = min

          do {
            result.push(ii)
            const previous = ii
            ii = parseFloat((ii + increment).toFixed(2))

            // Prevent infinite loops
            if (ii === previous) {
              break
            }
          } while (ii <= max)

          return result.map(value => ({ value, label: `${value} ${display_name}` }))
        }
      },
      ({ product, includeZeroOption }) => {
        // Use ordering parameters as the memoize hash
        return JSON.stringify({
          ordering_parameters: product?.ordering_parameters,
          includeZeroOption,
        })
      }
    )
  })
