import angular from 'angular'

export default angular
  .module('IcExpressComponentModule', [])
  .config(($stateProvider, configConstants, langProvider) => {
    const { icExpress } = configConstants
    const { mainPage, profilePage } = icExpress

    if (icExpress.enabled) {
      $stateProvider
        .state('main.icExpress', {
          url: `/${mainPage.urlName}`,
          views: {
            'content@': {
              template: `
                  <div class="ic-express-main">
                    <ic-express-entry
                      is-go-to-main-on-success="true"
                    ></ic-express-entry>
                  </div>
                `,
            },
          },
          onEnter($rootScope, $state, $timeout, auth, express, titleService) {
            $timeout(() => {
              titleService.setTitle(langProvider.values.icExpress.subscriptionPageTitle)

              // If the user is not logged in, the call to get subscriptions will fail.
              // Instead, navigate directly to the page
              if (!auth.user.ic_linked) {
                return
              }

              if (!icExpress.enabled) {
                $state.go('main.404')
                return
              }
              $rootScope.isAppLoading = true
              express
                .getCurrentSubscription(true)
                .then(() => {
                  // The subscription route is not available for express trial and membership users
                  if (express.isMember() || express.isTrial() || express.isCoupon()) {
                    $state.go('main.account.icExpress')
                  }
                })
                .catch(() => $state.go('main.404'))
                .finally(() => ($rootScope.isAppLoading = false))
            })
          },
        })

        .state('main.icExpressCancel', {
          url: `/${mainPage.urlName}/cancel`,
          views: {
            'content@': {
              template: `
                <ic-express-cancellation
                  ng-if="!isLoading"
                ></ic-express-cancellation>
              `,
              controller($rootScope, $scope, $state, express) {
                $scope.isLoading = true
                $rootScope.isAppLoading = true

                express
                  .getCurrentSubscription(true)
                  .catch(() => $state.go('main.404'))
                  .finally(() => {
                    $scope.isLoading = false
                    $rootScope.isAppLoading = false
                  })
              },
            },
          },
          onEnter($state, $timeout, auth, titleService) {
            $timeout(() => {
              titleService.setTitle(langProvider.values.icExpress.cancelPageTitle)

              if (!auth.user.ic_linked || !icExpress.enabled) {
                $state.go('main.404')
              }
            })
          },
        })

        .state('main.account.icExpress', {
          url: `/${profilePage.urlName}`,
          views: {
            'content@main.account': {
              controller: 'IcExpressProfilePageController',
              controllerAs: '$ctrl',
              template: icExpress.profilePage.showNewProfilePage
                ? require('./views/expressProfilePageNew.html')
                : require('./views/expressProfilePage.html'),
            },
          },
          data: {
            breadcrumbs: [
              {
                label: langProvider.values.accountDetails,
              },
              {
                label: icExpress.name,
              },
            ],
          },
          onEnter($rootScope, $state, $timeout, auth, express) {
            $timeout(() => {
              if (!auth.user.ic_linked || !icExpress.enabled) {
                $state.go('main.404')
                return
              }

              express
                .getCurrentSubscription(true)
                .then(({ subscription }) => {
                  if (!subscription || !subscription.id) {
                    $state.go('main.icExpress', null, { location: 'replace' })
                  }
                })
                .catch(() => $state.go('main.404'))
            })
          },
        })
    }
  })
