import angular from 'angular'

export default angular.module('ShoppingSelectorContextItem', []).component('shoppingContextItem', {
  bindings: {
    classIcons: '<',
    ctxName: '<',
    isActive: '<',
    showTransition: '<',
  },
  controller(config, lang) {
    this.id = `shopping-selector-shop-context-intent-${this.ctxName}`
    this.class = `li-el ${this.classIcons} icon ${this.ctxName}-context`
    this.title = config.shoppingSelector[this.ctxName].title
    this.selectedSrText = lang.values.selected
  },
  template: `
      <button
        id="{{ $ctrl.id }}"
        class="{{ $ctrl.class }}"
        ng-class="{ active: $ctrl.isActive, 'modal-transition': $ctrl.showTransition }"
        aria-current="{{ $ctrl.isActive }}"
      >
        <div class="shopping-context-list-title">
          <span>{{ $ctrl.title }}</span>
          <span ng-if="$ctrl.isActive" class="screen-reader-text">{{ $ctrl.selectedSrText }}</span>
        </div>
      </button>
    `,
})
