import angular from 'angular'

import appendMetaTagDescription from '@app/utils/appendMetaTagDescription'
import { SCRIPT_TIMEOUT } from '@app/angular/constants/personalizationLoading'

export default angular
  .module('HomeComponentModule', [])
  .config(
    ($ocLazyLoadProvider, $stateProvider, $urlRouterProvider, configConstants, langProvider) => {
      $stateProvider
        .state('main.home', {
          url: '/?collections',
          views: {
            'content@': {
              controller: 'HomeController',
              template: require('./home.html'),
            },
          },
          resolve: {
            loadCtrl: $ocLazyLoad => {
              return import(
                /* webpackChunkName: "homeController", webpackPrefetch: true */ './homeController'
              )
                .then(mod => $ocLazyLoad.load(mod.default))
                .catch(err => {
                  throw new Error(`Ooops, something went wrong, ${err}`)
                })
            },
            title(titleService) {
              return titleService.setTitle(langProvider.values.home)
            },
          },
          onEnter: lang => {
            appendMetaTagDescription(lang.values.metaTagDescription)
          },
        })
        // 'Pseudo route' to allow retailers to load their GTM loaded personalization script which will run, send an xhr request
        // to their personalization provider (Adobe), and wait for a list of collection id's to be returned. Once this is returned
        // they can route the user to the homepage, with this format i.e. '/?collections=$retailerCollectionArray'.

        // We put in a timeout in case the clients personalization script fails, in which case we route them to the default MLP
        .state('main.home.loading', {
          url: 'script-loading',
          resolve: {
            waitForRetailerScript($state, $timeout) {
              $timeout(
                () => {
                  $state.go('main.home')
                },
                configConstants.allowPassingCollectionIDAsQueryParams?.homepage ? SCRIPT_TIMEOUT : 0
              )
              return new Promise(() => {})
            },
          },
        })
      $urlRouterProvider.when('', '/')
    }
  )
  .run($templateCache => {
    $templateCache.put('home.desktop.html', require('./home.desktop.html'))
    $templateCache.put('home.mobile.html', require('./home.mobile.html'))
    $templateCache.put('homepageOrders.html', require('./homepageOrders.html'))
  })
