import angular from 'angular'
import { SOURCES } from '@app/angular/constants/payments'
import template from './creditCardList.html'

class CreditCardList {
  constructor(errorModalService, lang, mobileService, utilService) {
    Object.assign(this, {
      SOURCES,
      errorModalService,
      lang,
      mobileService,
      utilService,
    })
  }

  handleLockedCreditCardClick() {
    if (!this.mobileService.isMobile) return

    const { creditCardLocked, ok, warning } = this.lang.values

    this.errorModalService.open(creditCardLocked, ok, null, warning)
  }

  showDeleteLink(creditCard) {
    return (
      (this.allowDeleteOfSelected || this.selectedCreditCard.ext_id !== creditCard.ext_id) &&
      !creditCard.noSave
    )
  }

  formatCardExpiry(expDate) {
    const expDateStr = typeof expDate === 'number' ? expDate.toString() : ''
    return padStart(expDateStr, 2, '0')
  }

  getCardInfoString(creditCard) {
    const { disableCardName, lang } = this
    return lang.template(lang.values.cardEnding, {
      cardName: (!disableCardName && creditCard.name) || creditCard.provider,
      cardNumber: creditCard.last_four_digits,
    })
  }

  getCardExpiryString(creditCard) {
    const { lang } = this
    return lang.template(lang.values.cardExpiring, {
      month: this.formatCardExpiry(creditCard.exp_month),
      year: creditCard.exp_year,
    })
  }
}

export default angular.module('CreditCardListComponentModule', []).component('creditCardList', {
  template,
  bindings: {
    creditCards: '<', // Array<Object>: An array of credit card objects
    onDeleteClick: '&?', // Function? called for the delete click
    onSelect: '&?', // Function?: Called when a new card is selected
    selectedCreditCard: '=?', // Object Reference?: The "ng-model" for the selected card
    useIcTheme: '<?', // Boolean?: Whether to use the IC Theme
    disableSelectButton: '=?', // Boolean?: True if button for select should not be enabled
    disableDelete: '=?', // Boolean?: True if delete is disabled
    disableCardName: '=?', // Boolean?: True if card description name is disabled and should not show
    allowDeleteOfSelected: '=?', // Boolean?: True if allowed to delete selected
    includeExpiryDate: '=?', // Boolean?: True if to include expiry date
  },
  controller: CreditCardList,
  controllerAs: '$ctrl',
})

/*
 * TODO REMOVE THIS WHEN POLYFILL SUPPORT HAS BEEN ADDED
 * https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/padStart#Polyfill
 */
function padStart(sourceString, targetLength, padString) {
  targetLength = targetLength >> 0 // truncate if number, or convert non-number to 0;
  padString = String(typeof padString !== 'undefined' ? padString : ' ')
  if (sourceString.length >= targetLength) {
    return String(sourceString)
  }

  targetLength = targetLength - sourceString.length
  if (targetLength > padString.length) {
    padString += padString.repeat(targetLength / padString.length) // append to original to ensure we are longer than needed
  }
  return padString.slice(0, targetLength) + String(sourceString)
}
