import angular from 'angular'

export default angular
  .module('CollectionModalControllerModule', [])
  .controller(
    'CollectionModalController',
    function (
      $scope,
      $uibModal,
      Restangular,
      auth,
      cartService,
      config,
      endpoint,
      noticeAlert,
      utilService
    ) {
      $scope.auth = auth
      $scope.cartService = cartService
      $scope.config = config

      // Pagination

      $scope.page = { current: 1 }

      $scope.$watch('page.current', (newPage, oldPage) => {
        if (oldPage && newPage !== oldPage) {
          utilService.scrollElementToTop(document.querySelector('.collection-modal .modal-content')) // Hack
          delete $scope.items
          $scope.loadItems()
        }
      })

      // Load items from the API, if an endpoint is specified
      // (may not be, for subcategory view, etc..)

      if (endpoint) {
        $scope.loadItems = function () {
          const params = {
            offset: $scope.page.current ? ($scope.page.current - 1) * config.productsPerPage : 0,
            limit: config.productsPerPage,
          }
          Restangular.all(endpoint)
            .getList(params)
            .then(items => {
              $scope.items = items
            })
        }
        $scope.loadItems()
      }

      // Watch for state to change, closing the modal if it does

      $scope.$on('$stateChangeStart', () => {
        $scope.$close()
      })

      $scope.noticeAlert = noticeAlert
    }
  )
  .run($templateCache => {
    $templateCache.put(
      'pagination-modified-uibootstrap-desktop.html',
      require('../pagination/pagination-modified-uibootstrap-desktop.html')
    )
    $templateCache.put(
      'pagination-modified-uibootstrap-mobile.html',
      require('../pagination/pagination-modified-uibootstrap-mobile.html')
    )
  })
