import angular from 'angular'
import template from './preferredPhoneNumberSelect.html'

export default angular
  .module('PreferredPhoneNumberSelectComponentModule', [])
  .directive('preferredPhoneNumberSelect', function () {
    return {
      scope: {
        preferredPhoneNumber: '=',
        inputIdAttr: '@?',
        disabled: '@?',
      },
      replace: true,
      template,
      controller($scope, config, lang) {
        $scope.config = config
        $scope.lang = lang
        $scope.phoneNumberFields = config.deliveryAddressPhoneNumberFields

        $scope.labels = {}
        config.deliveryAddressPhoneNumberFields.forEach(field => {
          $scope.labels[field.key] = field.label
        })
      },
    }
  })
