// Intercept clicks on an element if the disable expression is falsy

import angular from 'angular'

export default angular.module('DisableComponentModule', []).directive('disable', function () {
  return {
    restrict: 'A',
    scope: {
      disable: '=',
    },
    link(scope, el) {
      el.on('click', e => {
        if (scope.disable) {
          e.preventDefault()
        }
      })

      scope.$watch('disable', () => {
        if (scope.disable) {
          el.addClass('disable')
        } else {
          el.removeClass('disable')
        }
      })
    },
  }
})
