import _ from 'lodash'
import angular from 'angular'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { MOBILE_HEADER_CLICKED } from '@app/angular/constants/navigation'

import template from './sideCartSubMenu.html'

export default angular
  .module('unataSideCartSubMenuComponentModule', [])
  .component('unataSideCartSubMenu', {
    bindings: {
      sortList: '&',
      currentSortType: '<',
      emptyList: '&',
      showNotesSubstitutions: '=',
      sortedCategories: '<',
      sortedProducts: '<',
      uncheckAllItems: '&',
      deleteCheckedItems: '&',
      checkedItemsCount: '<',
    },
    controller(
      $rootScope,
      $scope,
      $state,
      $attrs,
      $timeout,
      auth,
      cartService,
      config,
      lang,
      mobileService
    ) {
      this.$onInit = () => {
        // Close the dropdown menu when clicking on the mobile header (currently not covered by the menu backdrop)
        this.mobileHeaderClickDeregister = $rootScope.$on(
          MOBILE_HEADER_CLICKED,
          () => (this.isMenuOpen = false)
        )
      }
      this.$onDestroy = () => {
        this.mobileHeaderClickDeregister()
      }

      this.mobileService = mobileService
      this.lang = lang
      this.auth = auth
      this.cartService = cartService
      this.config = config

      this.isMenuOpen = false
      this.menuOpenedBy = undefined

      this.menuConfig =
        this.auth.getContext().intent === SHOPPING_MODES.INSTORE_LIST
          ? this.config.userList
          : this.config.cartReview

      this.inShoppingList =
        $state.current.name === 'main.inStoreList.review' || $state.current.name === 'offlineList'
      this.inShoppingListContext = this.auth?.getContext()?.intent === SHOPPING_MODES.INSTORE_LIST
      this.inInstoreListPage = $state.current.name === 'main.sideCart' || this.inShoppingList

      this.inCheckoutCartPage = $state.current.name === 'main.checkout.cart'

      this.toggleMenu = () => {
        this.isMenuOpen = !this.isMenuOpen

        if (this.isMenuOpen) {
          this.menuOpenedBy = document.activeElement
          $timeout(() => {
            document.querySelector('.side-sub-menu-close-button')?.focus()
          })
        }

        // a11y: Re-focus on the element that opened the dialog
        if (!this.isMenuOpen && this.menuOpenedBy) {
          this.menuOpenedBy.focus()
        }
      }

      this.triggerSortModal = () => {
        this.sortList()
      }

      this.toggleShowNotesSubs = () => {
        this.showNotesSubstitutions = !this.showNotesSubstitutions
      }

      if (this.cartService.cart) {
        this.getCustomItems = () =>
          _.filter(this.cartService.cart.items, { item_type: 'pseudo_product' })
      }
    },
    template,
  })
