/*
 * For a list of payment providers and their compatibility, please see:
 * https://instacart.atlassian.net/wiki/spaces/ENG/pages/995098979/Payment+Providers+and+new+checkout+styling
 *
 * For each payment provider service, you'll want to set the css class it should use with the new checkout (paymentForm.styl):
 *    paymentGatewayProvider.registerGateway({
 *      name: ...
 *      ...
 *      ...
 *      newCheckoutCssClass: 'provider-generic_styling'   // This is useful for most, but use a custom style if need be!
 *    });
 */
import angular from 'angular'

import template from './updateCreditCard.html'

class UpdateCreditCard {
  constructor($q, $scope, config, lang, mobileService, paymentGateway) {
    Object.assign(this, {
      config,
      lang,
      isNewCard: false,
      paymentGateway,
    })

    this.services = {
      $q,
      $scope,
      mobile: mobileService,
    }

    Object.assign($scope, {
      toggleIsShowingAddCard: isNewCard => (this.isNewCard = isNewCard),
      lang,
    })
  }

  $onInit() {
    this.services.$scope.payment = this.payment
    this.selectedGateway = this.paymentGateway.getTemplate(this.paymentType)
    this.gatewayInformation = this.paymentGateway.get(this.paymentType)
    this.gatewayService = this.paymentGateway.getService(this.paymentType)
    this.paymentCssClassName = this.gatewayInformation.newCheckoutCssClass
  }

  shouldShowSaveButton() {
    return !this.gatewayInformation?.hideSaveButton && !this.isNewCard
  }

  doSave() {
    const {
      gatewayService,
      onSave,
      services: {
        $q,
        $scope: { payment },
      },
    } = this

    $q.resolve()
      .then(() => gatewayService.verifyPayment && gatewayService.verifyPayment())
      .then(() => onSave({ payment }))
      .catch(error => onSave({ error }))
  }
}

export default angular.module('UpdateCreditCardModule', []).component('updateCreditCard', {
  bindings: {
    fulfillmentType: '<',
    deliveryAddress: '<',
    billingAddress: '<',
    payment: '<',
    paymentType: '<',
    onSave: '&',
  },
  template,
  controller: UpdateCreditCard,
})
