import angular from 'angular'

export default angular
  .module('ElementInViewComponentModule', [])
  .directive('elementInView', function ($document, $timeout) {
    return {
      restrict: 'A',
      scope: {
        inView: '=',
      },
      link: (scope, el) => {
        const checkIfInView = () => {
          const elementBounds = el[0].getBoundingClientRect()
          const elementIsInViewport =
            elementBounds.top >= 0 &&
            elementBounds.left >= 0 &&
            elementBounds.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
            elementBounds.right <= (window.innerWidth || document.documentElement.clientWidth)

          // $timeout needed to trigger Angular digest cycle to adjacent scopes
          $timeout(() => {
            scope.inView = elementIsInViewport
          })
        }

        $document.on('scroll', checkIfInView)
        scope.$on('$destroy', () => {
          $document.off('scroll', checkIfInView)
        })

        checkIfInView()
      },
    }
  })
