import angular from 'angular'
import template from './shopModalHeader.html'

export default angular
  .module('ShopModalHeaderComponentModule', [])
  .directive('shopModalHeader', function ($rootScope, config, oneCartService) {
    return {
      template,
      link(scope) {
        const nextStep = () => {
          $rootScope.$broadcast('sideCart', 'open')
          scope.$dismiss()
        }
        scope.goToNextStep = () => (oneCartService.enabled() ? nextStep : undefined)
      },
    }
  })
