/**
 * A factory that returns a function that wraps the original function. It calls the original function once, but then
 * returns the same returned result (in a promise) every subsequent time, regardless of args or changes.
 *
 * If the original function returns a promise that fails, the original function will be called again on next call
 *
 * @param {function} fn                    - The original function (preferably returning a promise)
 * @returns {function}                     - Wrapped function with promise memoisation
 *
 * You can call wrapperFn.resetPromiseable() to forcefully remove the cached promise so that on next call the original
 * function will be called again.
 *
 * @example
 *   promiseable(
 *     async (arg1, arg2) => {
 *        return doSomeCodeStuff(arg1, arg2);
 *     }
 *   );
 */
export default fn => {
  const wrapper = (...args) => {
    if (fn._result) return fn._result

    try {
      fn._result = Promise.resolve(fn(...args)).catch(e => resetAndReject(e, fn))
      return fn._result
    } catch (e) {
      return resetAndReject(e, fn)
    }
  }

  wrapper.resetPromiseable = () => resetAndReject(null, fn)

  return wrapper
}

function resetAndReject(e, fn) {
  fn._result = null
  return Promise.reject(e)
}
