import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('facetedSearchServicesModule', [])
  .service(
    'facetedSearchService',
    function facetedSearchService(
      $q,
      $state,
      $stateParams,
      Restangular,
      config,
      tagsService,
      utilService
    ) {
      const service = this

      service.categorizedFacetsObject = {}

      service.hideFacets = true

      // Not quite breadcrumbs, but keeps track of tags for each facet currently being filtered,
      // allowing users to delete per tag or reset fully in the "tag crumbs" component in the UI
      service.tagCrumbsArray = []
      service.tagGroupDisplayNameArray = []

      // We need to pass `group_display_name` for all queries that have the `tags` query param
      service.setGroupDisplayName = (selected, selectedTagGroupDisplayName) => {
        const selectedGroupDisplayNameIndex = service.tagGroupDisplayNameArray.indexOf(
          selectedTagGroupDisplayName
        )

        if (selected && selectedGroupDisplayNameIndex < 0) {
          service.tagGroupDisplayNameArray.push(selectedTagGroupDisplayName)
        } else if (!selected && selectedGroupDisplayNameIndex > -1) {
          service.tagGroupDisplayNameArray.splice(selectedGroupDisplayNameIndex, 1)
        }

        return service.tagGroupDisplayNameArray
      }

      service.setTagCrumbs = (selected, tag, tagType) => {
        if ($stateParams.tags || $stateParams.brand_name || $stateParams.brand_names) {
          const selectedTagCrumb = {
            type: tagType,
            name: tag,
            display_name: tagsService.byVal(tag) ? tagsService.byVal(tag).name : tag,
          }

          const selectedTagCrumbIndex = _.findIndex(service.tagCrumbsArray, selectedTagCrumb)

          if (selected && selectedTagCrumbIndex < 0) {
            service.tagCrumbsArray.push(selectedTagCrumb)
          } else if (!selected && selectedTagCrumbIndex > -1) {
            service.tagCrumbsArray.splice(selectedTagCrumbIndex, 1)
          }

          // // Remove any tags or brands that are not in $stateParams
          const cloneArray = angular.copy(service.tagCrumbsArray)
          service.tagCrumbsArray = _.remove(cloneArray, tagCrumb => {
            return (
              _.includes($stateParams.tags, tagCrumb.name) ||
              _.includes($stateParams.brand_name, tagCrumb.name) ||
              _.includes($stateParams.brand_names, tagCrumb.name)
            )
          })

          return service.tagCrumbsArray
        }
      }

      service.generateTagCrumbsQueryParams = () => {
        const brandNamesQueryParams = _.filter(service.tagCrumbsArray, tagCrumb => {
          return tagCrumb.type === 'brand_name'
        }).map(tag => tag.name)

        const tagsQueryParams = _.filter(service.tagCrumbsArray, tagCrumb => {
          return tagCrumb.type === 'tags'
        }).map(tag => tag.name)

        return {
          brandNames: brandNamesQueryParams,
          tags: tagsQueryParams,
        }
      }

      service.removeTagCrumbByIndex = index => {
        service.tagGroupDisplayNameArray.splice(index, 1)
        service.tagCrumbsArray.splice(index, 1)
        return service.tagCrumbsArray
      }

      service.resetTagCrumbs = () => {
        service.tagGroupDisplayNameArray = []
        return (service.tagCrumbsArray = [])
      }

      service.categorizeFacets = facetsArray => {
        // Exclude any filters defined in the facets config
        const filteredFacetsArray = facetsArray.filter(
          facet =>
            !config.facets.excludeSideFiltersByGroupDisplayName.includes(facet.group_display_name)
        )

        const brands = _.filter(filteredFacetsArray, facet => {
          return facet.group_display_name === 'Brands'
        })[0]
        const categories = _.filter(filteredFacetsArray, facet => {
          return facet.group_display_name === 'Categories'
        })[0]
        const sideFilters = _.filter(filteredFacetsArray, facet => {
          return facet.group_priority >= 1
        })

        const allowedBrands = config.facets.allowedBrands
        if (allowedBrands) {
          brands.items = brands.items.filter(item => allowedBrands.includes(item.name))
        }

        return {
          brands,
          categories,
          sideFilters,
        }
      }

      service.getAllFacets = params => {
        // The facets call was originally intended to take a list of category_ids, this
        // may still be implemented in future. For now, map the single category_id value
        // to category_ids and send that instead.
        params.category_ids = params.category_id

        const facetedParams = _.omit(params, ['category_id'])

        // If facets is disabled or user is searching through a collection, kick out of this function.
        if (!config.enableFacetedSearchFilters && !config.enableICSearch) {
          return $q.resolve()
        }
        return Restangular.all('store_products')
          .all('facets')
          .getList(facetedParams)
          .then(results => {
            const hideFacets = results.default
            const facets = results.plain()

            const sortedFacets = facets.sort((a, b) => {
              const priorityA = a.group_priority || 0
              const priorityB = b.group_priority || 0

              if (priorityA < priorityB) {
                return -1
              }
              if (priorityA > priorityB) {
                return 1
              }
              return 0
            })
            service.categorizedFacetsObject = service.categorizeFacets(sortedFacets)
            // Hide facets on flyer categories, otherwise depend on the results of the call above.
            service.hideFacets =
              $state.current.name === 'main.shop.flyer.category' ? true : hideFacets

            // Baseline facet groups should always be sorted by priority
            return { sortedFacets, hideFacets: service.hideFacets }
          })
      }

      service.getFacetDisplayName = name => {
        const result = utilService.getObjects(service.categorizedFacetsObject.sideFilters, '', name)
        return result[0]?.display_name || name
      }

      service.getTagName = tag => {
        const tagTag = tagsService.byVal(tag)
        if (config.enableFacetedSearchFilters || config.enableICSearch) {
          const facetTag = service.getFacetDisplayName(tag)
          if (facetTag !== tag) {
            return facetTag
          }
          return tagTag?.name || tag
        }
        return tagTag?.name || tag
      }
    }
  )
