import angular from 'angular'
import fullfillmentNotAvailableModal from '@app/angular/components/fulfillmentNotAvailableModal/fulfillmentNotAvailableModal.html'
import { SHOPPING_SELECTOR_STAGES } from '@app/angular/constants/shoppingSelectorStages'
import { SHOPPING_MODES } from '@app/angular/constants/context'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'

export default angular
  .module('fulfillmentNotAvailableModalModule', [])
  .service(
    'fulfillmentNotAvailableModalService',
    function fulfillmentNotAvailableModalService($uibModal, legacyModalService) {
      const service = this

      service.open = () => {
        legacyModalService.closeOpenModals()
        return $uibModal.open({
          template: fullfillmentNotAvailableModal,
          controller: [
            '$scope',
            'auth',
            'shoppingContextService',
            'shoppingSelectorService',
            ($scope, auth, shoppingContextService, shoppingSelectorService) => {
              $scope.close = () => $scope.$close()
              $scope.continue = () => {
                $scope.$close()
                auth
                  .setIntent(SHOPPING_MODES.DELIVERY)
                  .then(() =>
                    shoppingContextService.setSelectedFulfillmentType(SHOPPING_MODES.DELIVERY)
                  )
                  .then(() => shoppingSelectorService.open(SHOPPING_SELECTOR_STAGES.STORE_SELECTOR))
              }
            },
          ],
          controllerAs: '$ctrl',
        }).result
      }

      eventEmitter.on(EVENT_TYPES.OPEN_FULFILLMENT_NOT_AVAILABLE_MODAL, () => {
        service.open()
      })
    }
  )
