import angular from 'angular'
import _ from 'lodash'
import { shoppingList as shoppingListService } from '@instacart/enterprise-services'
import { IC_PLACEMENTS } from '@app/angular/constants/express'
import { FULFILLMENT_TYPES, CHECKOUT_STEP_IDS } from '@app/angular/constants/checkout'
import template from './cartSummary.html'

class CartSummary {
  constructor(
    $filter,
    $scope,
    cartService,
    config,
    express,
    lang,
    checkoutService,
    mobileService,
    orderService,
    reactCartLoyaltyBalanceDefaultProps,
    userService,
    utilService,
    loyaltyService,
    orderFeesService
  ) {
    const { minimumOrderAmount } = config.icExpress
    const checkoutPlacementEnabled = express.isPlacementConfigEnabled(IC_PLACEMENTS.CHECKOUT)

    Object.assign(this, {
      $scope,
      loyaltyService,
      currencyFilter: $filter('currency'),
      cartService,
      checkoutPlacementEnabled,
      checkoutService,
      config,
      express,
      fees: [() => this.cart.additional_fees, () => this.cart],
      getFeeLabel: fee => orderService.fulfillmentLabel(fee, this.fulfillmentType),
      getMinCartSpend: shoppingListService.commonUtils.getMinimumCartSpend,
      hasIcExpressOptedIn: false,
      lang,
      loyaltyReactProps: reactCartLoyaltyBalanceDefaultProps,
      minimumOrderAmount,
      mobileService,
      showLoyaltyCardBalance: () =>
        userService.loyaltyCards.length > 0 &&
        // eslint-disable-next-line no-restricted-globals
        !isNaN(userService.getLoyaltyCard().points_balance) &&
        config.hasLoyalty &&
        config.showLoyaltyBalanceInCart,
      userCurrentSubscription: null,
      userService,
      utilService,
      orderFeesService,
      rewards: [],
      orderTotalsLines: [],
    })
  }

  $onInit() {
    // Fetches user current express subscription if necessary, and passes it down to components
    if (this.config.icExpress.enabled) {
      this.express
        .getCurrentSubscription()
        .then(subscription => (this.userCurrentSubscription = subscription))
    }

    if (this.config.rewards.visible.length > 0) {
      this.$scope.$watch(
        () => this.cartService.syncing,
        () => this._setRewards()
      )
    }
  }

  syncAndValidate() {
    return this.cartService.sync({
      validate: this.checkoutService.isCartValidated,
      validatePayload: {
        with_fees: true,
        ...this.checkoutService.getValidationPayload(this.fulfillmentType),
      },
    })
  }

  getTipCopy() {
    return this.fulfillmentType === FULFILLMENT_TYPES.PICKUP
      ? this.lang.values.tipPickup
      : this.lang.values.tipGoesToDriver
  }

  getOrderTotalsLines() {
    // Add appropriate order totals lines
    let setOrderTotalsLines = [
      {
        type: 'value',
        label: this.lang.values.itemTotal,
        value: this.cartTotals.pre_discount_product_total,
      },
    ]

    if (!this.cartService.isCartBelowMin()) {
      if (this.showCreditsApplied()) {
        setOrderTotalsLines.push({
          type: 'value',
          label: this.lang.values.creditsApplied,
          value: this.cart.credit_total,
        })
      }

      if (this.showOfferDiscount()) {
        setOrderTotalsLines.push({
          type: 'value',
          label: this.lang.values.offerDiscount,
          value: -this.cart.offer_total,
        })
      }

      if (this.rewards.length) {
        setOrderTotalsLines.push({
          type: 'divider',
        })

        this.rewards.forEach(reward => {
          setOrderTotalsLines.push({
            type: 'value',
            label: reward.altLabel,
            description: reward.primaryInfo,
            value: reward.amount,
          })
        })
      }

      if (this.checkoutService.isCartValidated) {
        if (this.rewards.length) {
          setOrderTotalsLines.push({
            type: 'divider',
          })
        }

        setOrderTotalsLines.push({
          type: 'value',
          label: this.lang.values.tax,
          value: this.cartService.getFinalTaxTotal(),
        })

        setOrderTotalsLines.push({
          type: 'divider',
        })

        setOrderTotalsLines = setOrderTotalsLines.concat(
          this.orderFeesService.formattedFees(this.fees, this.fulfillmentType)
        )
      }
    }

    // Ensures that a digset cycle is only triggered if the lines have changed.
    if (!_.isEqual(setOrderTotalsLines, this.orderTotalsLines)) {
      this.orderTotalsLines = setOrderTotalsLines
    }

    return this.orderTotalsLines
  }

  openPromoCodeModal() {
    this.utilService.openPromoCodeModal(
      this.syncAndValidate.bind(this),
      this.mobileService.isMobile,
      true
    )
  }

  _setRewards() {
    this.rewards = this.loyaltyService.getVisibleRewards(
      this.cartService.cart?.cart_totals?.[this.cartService.getCurrentCartType()]?.reward_discounts
    )
  }

  get placeOrderValidationLabel() {
    if (!this.currentEditStep) return ''

    const { values, template } = this.lang
    const intent =
      this.fulfillmentType === FULFILLMENT_TYPES.PICKUP ? values.pickup : values.delivery

    switch (this.currentEditStep) {
      case CHECKOUT_STEP_IDS.PICKUP:
        return values.pleaseSelectPickup
      case CHECKOUT_STEP_IDS.SCHEDULE:
        return template(values.pleaseSelectTimeslot, { fulfillmentType: intent })
      case CHECKOUT_STEP_IDS.CONTACT:
        return values.pleaseFillContactInfo
      case CHECKOUT_STEP_IDS.PAYMENT:
        return values.pleaseSelectPaymentMethod
      case CHECKOUT_STEP_IDS.DELIVERY:
        return values.pleaseSelectDeliveryAddress
    }

    return ''
  }

  /**
   * Returns the cart totals object depending on the user current cart type
   *
   * @returns { object }
   */
  get cartTotals() {
    return this.cartService.getCartTotals()
  }

  /**
   * Returns whether the client currently has a client or instacart express subscription
   *
   * @returns { boolean }
   */
  hasClientSubscription() {
    const total = this.cart?.product_total

    return (
      this.cartService.cart.subscription_total !== '0' ||
      ((this.express.isIcExpressOptedIn || this.express.hasSubscription()) &&
        total &&
        total >= this.minimumOrderAmount)
    )
  }

  /**
   * Callback that executes after a tip is susscessfully applied to the cart
   *
   * @returns { Promise }
   */
  onTipApplied() {
    return this.cartService.validateCart({
      with_fees: true,
      ...this.checkoutService.getValidationPayload(this.fulfillmentType),
    })
  }

  /**
   * Returns true if we should show the subscription selection
   *
   * @returns { boolean }
   */
  showSubscriptionSelection() {
    return (
      // Make sure the user is in delivery or pickup fulfillment type
      (this.fulfillmentType === FULFILLMENT_TYPES.DELIVERY ||
        this.fulfillmentType === FULFILLMENT_TYPES.PICKUP) &&
      // Make sure that the subscriptions feature is enabled
      this.config.subscriptions.enabled &&
      // Make sure that the cartCheckout feature is also enabled
      this.config.subscriptions.cartCheckout.enabled &&
      // Check that purchasing for subscriptions is enabled
      this.config.subscriptions.isPurchasingEnabled
    )
  }

  /**
   * Returns true if the offer discount should be shown.
   *
   * @returns { boolean }
   */
  showOfferDiscount() {
    return (
      this.config.showOfferDiscountAndCreditsApplied || parseInt(this.cart.offer_total, 10) !== 0
    )
  }

  /**
   * Returns true if the credit applied should be shown.
   *
   * @returns { boolean }
   */
  showCreditsApplied() {
    return (
      this.config.showOfferDiscountAndCreditsApplied || parseInt(this.cart.credit_total, 10) !== 0
    )
  }
}

export default angular.module('CartSummaryModule', []).component('cartSummary', {
  template,
  bindings: {
    cart: '<',
    fulfillmentType: '<',
    isPlaceOrderEnabled: '<',
    totalLabelText: '<',
    currentEditStep: '<',
    condensed: '<',
    onPlaceOrder: '&',
    isPlacingOrder: '<',
    cartType: '<',
  },
  controller: CartSummary,
})
