import React, { Fragment } from 'react'
import {
  Checkbox,
  Text,
  createStyles,
  utils,
  GridContainer,
  GridItem,
  FormLabel,
} from '@instacart/cocktail'
import { lang as langService } from '@instacart/enterprise-services'

// TODO: use cocktail component for radio button once it is ready
// TODO: use a dropdown for when there is more than 2 subscription options

import PropTypes from 'prop-types'

const useStyles = props =>
  createStyles(
    tokens => ({
      root: {
        width: '100%',
        backgroundColor: tokens.color.neutral.grey1,
        padding: tokens.size.layout.xxSmall,
      },
      title: {
        color: tokens.color.text.primary,
        fontWeight: tokens.font.weight.bold,
      },
      selectOption: {
        marginTop: tokens.size.layout.xxSmall,
      },
      radioButton: {
        flex: '0 0 auto',
        height: tokens.size.checkbox.height.regular,
        width: tokens.size.checkbox.height.regular,
      },
      radioLabel: {
        color: tokens.color.text.primary,
        fontSize: tokens.size.font.caption,
        verticalAlign: 'middle',
        display: 'flex',
      },
      radioLabelText: {
        paddingTop: (tokens.size.checkbox.height.regular - tokens.size.font.body) / 2,
        display: 'inline-block',
      },
      subscribedIcon: {
        height: tokens.size.checkbox.height.regular,
        width: tokens.size.checkbox.height.regular,
        marginRight: tokens.size.spacing.xxSmall,
        backgroundImage: `url(${props.config.cartCheckout.subscribedIcon})`,
      },
    }),
    { displayName: 'CartCheckoutSubscriptionSelect' }
  )(props)

const getSubscriptionFromCart = cartItems => {
  const foundCartItem =
    cartItems &&
    cartItems.find(cartItem => {
      return cartItem.store_product.product_type === 'subscription'
    })
  return foundCartItem
}

const defaultLang = {
  title: 'Yes, I want unlimited FREE Pick Up!',
  subtitle:
    "For only $49/year, get unlimited Pick Up services for a full year. View <a href='instacart.com' target='_blank'>Terms and Conditions.</a>",
  selectOption: 'Select Option:',
  subscribedTitle: 'You get FREE Pick Up!',
  subscribedSubtitle: 'Thank you for being a Fresh Express member.',
  ariaLabels: {
    toggleSubscriptions: 'Toggle subscriptions',
    addSubscription: 'Add {{subscriptionName}}',
  },
}

const CartCheckoutSubscriptionSelect = props => {
  const {
    config,
    i18n,
    subscriptions,
    onSubscriptionSelected,
    // TODO (deprecated after 2.26): cartItems is not needed as we won't be using the cart to buy subscriptions anymore
    cartItems,
    styles: originalStyles,
    isDisabled,
    userSubscription,
    selectedSubscription,
    ...restProps
  } = props
  const styles = useStyles(props)

  const lang = { ...defaultLang, ...i18n }

  const subscriptionInCart = getSubscriptionFromCart(cartItems)

  const passOldSubscription = ({ newSubscription }) => {
    onSubscriptionSelected({
      selectedSubscription: newSubscription,
      // TODO (deprecated after 2.26): subscriptionInCart is not needed as we won't be using the cart to buy subscriptions anymore
      oldSubscription: subscriptionInCart || selectedSubscription,
    })
  }

  const checkboxClicked = event => {
    if (event.target.checked) {
      passOldSubscription({ newSubscription: subscriptions && subscriptions[0] })
    } else {
      passOldSubscription({ newSubscription: null })
    }
  }

  const listItems = (
    <div>
      <Text css={styles.selectOption} variant="caption" color="textPrimary" bold>
        {lang.selectOption}
      </Text>
      <div>
        {subscriptions &&
          subscriptions.map(sub => (
            <div key={sub.id}>
              <label htmlFor={sub.id} css={styles.radioLabel}>
                <input
                  type="radio"
                  name="selectedSubscription"
                  defaultChecked={
                    // TODO (deprecated after 2.26): subscriptionInCart is not needed as we won't be using the cart to buy subscriptions anymore
                    (subscriptionInCart && subscriptionInCart.store_product.id === sub.id) ||
                    selectedSubscription?.id === sub.id
                  }
                  onClick={() => passOldSubscription({ newSubscription: sub })}
                  id={sub.id}
                  disabled={isDisabled}
                  css={styles.radioButton}
                  aria-label={langService.interpolate(lang.ariaLabels.addSubscription, {
                    subscriptionName: sub.name,
                  })}
                />
                {/* // TODO (deprecated after 2.26): sub.base_price is not needed as 'sub_price' is only for store products */}
                <div css={styles.radioLabelText}>
                  {sub.name}
                  {
                    // eslint-disable-next-line react/jsx-no-literals
                    ' - '
                  }
                  {utils.formatCurrency(sub.base_price || sub.price)}
                </div>
              </label>
            </div>
          ))}
      </div>
    </div>
  )

  const isNotSubscribed = (
    <Fragment>
      <GridItem>
        <Checkbox
          id="subscription-checkbox"
          // TODO (deprecated after 2.26): subscriptionInCart is not needed as we won't be using the cart to buy subscriptions anymore
          defaultChecked={subscriptionInCart || selectedSubscription}
          onChange={checkboxClicked}
          disabled={isDisabled}
          aria-label={lang.ariaLabels.toggleSubscriptions}
        />
      </GridItem>
      <GridItem>
        <FormLabel htmlFor="subscription-checkbox" css={styles.title}>
          {lang.title}
        </FormLabel>
        <Text
          variant="caption"
          color="textPrimary"
          dangerouslySetInnerHTML={{ __html: lang.subtitle }}
        />
        {/* // TODO (deprecated after 2.26): subscriptionInCart is not needed as we won't be using the cart to buy subscriptions anymore */}
        {subscriptions &&
          subscriptions.length > 1 &&
          (subscriptionInCart || selectedSubscription) &&
          listItems}
      </GridItem>
    </Fragment>
  )

  const isSubscribed = (
    <Fragment>
      <GridItem>
        {config && config.cartCheckout && config.cartCheckout.subscribedIcon && (
          <div css={styles.subscribedIcon} />
        )}
      </GridItem>
      <GridItem>
        <Text color="textPrimary" bold>
          {lang.subscribedTitle}
        </Text>
        <Text variant="caption" color="textPrimary">
          {lang.subscribedSubtitle}
        </Text>
      </GridItem>
    </Fragment>
  )

  return (
    config &&
    config.enabled &&
    config.cartCheckout.enabled && (
      <GridContainer
        css={styles.root}
        {...restProps}
        align="flex-start"
        wrap="nowrap"
        spacing="none"
      >
        {userSubscription ? isSubscribed : isNotSubscribed}
      </GridContainer>
    )
  )
}

CartCheckoutSubscriptionSelect.defaultProps = {}

CartCheckoutSubscriptionSelect.propTypes = {
  /** Styles to override the default styles */
  // eslint-disable-next-line react/forbid-prop-types
  styles: PropTypes.object,
  /** Configs to pass to the components */
  // eslint-disable-next-line react/forbid-prop-types
  config: PropTypes.object,
  /** Language overrides */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Array of subscriptions to pick from */
  // eslint-disable-next-line react/forbid-prop-types
  subscriptions: PropTypes.array,
  // TODO (deprecated after 2.26): Not needed as we won't be using the cart to buy subscriptions anymore
  /** Array of items currently in the cart */
  // eslint-disable-next-line react/forbid-prop-types
  cartItems: PropTypes.array,
  /** Function to when a new subscription is selected */
  onSubscriptionSelected: PropTypes.func,
  /** Boolean to disable the component */
  isDisabled: PropTypes.bool,
  /** Subscription object that is currently on the user */
  // eslint-disable-next-line react/forbid-prop-types
  userSubscription: PropTypes.object,
  /** Subscription object that has been selected and is waiting to be ordered */
  // eslint-disable-next-line react/forbid-prop-types
  selectedSubscription: PropTypes.object,
}

export default CartCheckoutSubscriptionSelect
