import angular from 'angular'
import dayjs from 'dayjs'
import isMobileApp from '@app/utils/isMobileApp'
import template from './loyaltyCard.html'

export default angular
  .module('LoyaltyCardComponentModule', [])
  .directive('loyaltyCard', function () {
    return {
      scope: {
        card: '=',
        loyaltyPass: '=',
        printable: '=?',
        useBarcodeNumberLabel: '=?',
      },
      replace: true,
      template,
      controller(
        $compile,
        $document,
        $scope,
        $state,
        $templateCache,
        $timeout,
        $q,
        $window,
        config,
        errorModalService,
        lang,
        mobileService,
        userService,
        utilService
      ) {
        const DEFAULT_EXPIRY_DATE_FMT = 'MMM D, YYYY'

        $scope.config = config
        $scope.lang = lang
        $scope.mobile = mobileService
        $scope.util = utilService

        if ($scope.printable === undefined || $scope.printable === null) {
          $scope.printable = !!config.printableLoyaltyCard
        }

        const cardImgUrl = utilService.loadAssetImage('images/loyalty-card-bg-design.png')
        const windowLocation = window.location
        const origin = `${windowLocation.protocol}//${windowLocation.hostname}${
          windowLocation.port ? `:${windowLocation.port}` : ''
        }`

        // origin needed for printing card. Image breaks in mobile app if origin is there
        $scope.cardImgUrl = isMobileApp() ? cardImgUrl : `${origin}/${cardImgUrl}`

        $scope.render = function () {
          let progress
          let pointsRemainder
          const deferred = $q.defer()

          if (angular.isDefined($scope.card)) {
            pointsRemainder = $scope.card.points_balance % $scope.card.points_increment
            $scope.pointsRedemptionDeficit = $scope.card.points_increment - pointsRemainder
            progress = (pointsRemainder / $scope.card.points_increment) * 100
          }

          if (config.hasLoyaltyIncrement && $scope.card) {
            progress = (pointsRemainder / $scope.card.points_increment) * 100

            $timeout(() => {
              // Wait for dom to be ready
              Circles.create({
                id: 'loyalty-progress',
                radius: 100,
                value: progress,
                width: 27,
                colors: [],
                text: '',
                duration: 400,
              })

              // Override some attributes on the SVG so we can make it responsive.

              const svg = document.querySelector('#loyalty-progress svg')
              // If we want to override the widge of this via css, we'll need to
              // strip the inline css attributes
              // svg.removeAttribute('width');
              // svg.removeAttribute('height');
              svg.setAttribute('viewBox', '0 0 200 200')
              return deferred.promise
            }, 0)
          } else {
            return deferred.promise
          }
        }

        $scope.render()

        // Go to Loyalty Pass url

        $scope.openInWallet = function () {
          // Open the loyalty pass in a new tab/windows on mobile web and in the system browser on cordova
          $window.open($scope.loyaltyPass.url, isMobileApp() ? '_system' : '_blank')
        }

        $scope.addToWallet = function () {
          $scope.updating = true
          userService
            .createLoyaltyPass()
            .then(() => userService.fetchLoyaltyPasses())
            .catch(response => {
              $scope.updating = false
              errorModalService
                .open(
                  response?.message || config.lang.defaultErrorMessage,
                  'Reload',
                  'Back To Home'
                )
                .then(() => {
                  $state.go($state.current, null, { location: 'replace' })
                })
                .catch(() => {
                  $state.go('main.home')
                })
            })
            .then(() => {
              $scope.loyaltyPass = userService.loyaltyPasses?.[0]
              $scope.updating = false
            })
        }

        $scope.printCard = function () {
          const className = 'printing'
          $document[0].body.classList.add(className)

          const loyaltyCard = document.querySelector('[loyalty-card]').cloneNode(true)
          loyaltyCard.classList.add('printable')
          $document[0].body.appendChild(loyaltyCard)

          try {
            // window.print() doesn't work on Safari in some circumstances,
            // neither blocks the executions.
            document.execCommand('print', false, null)
          } catch {
            window.print()
          }

          loyaltyCard.remove()

          $document[0].body.classList.remove(className)
        }

        $scope.getCardExpiryDate = function (format = DEFAULT_EXPIRY_DATE_FMT) {
          if ($scope.card?.expiry_timestamp) {
            return dayjs($scope.card.expiry_timestamp).format(format)
          }
          // no expiry date found
          return ''
        }
      },
    }
  })
  .run($templateCache => {
    $templateCache.put('loyaltyCard.html', require('./loyaltyCard.html'))
  })
