import angular from 'angular'
import ShoppingSelectorServiceModule from './shoppingSelectorService'

export default angular
  .module('ShoppingSelectorComponentModule', [ShoppingSelectorServiceModule.name])
  .config(($stateProvider, $urlRouterProvider) => {
    $urlRouterProvider.when('/shopping-mode', ($state, shoppingSelectorService) => {
      $state.go('main.home').then(() => {
        shoppingSelectorService.open('shoppingContext')
      })
    })
  })
