import angular from 'angular'

export default angular
  .module('anonymousIdServicesModule', [])

  // Exposes the user's anonymous id and adds the ability to set and update it on the backend.
  .service('anonymousIdService', function ($injector, $q, Restangular, config) {
    const service = this
    const LS_ANONYMOUS_KEY = 'una_anonymous_id'

    // Initialize this service's user
    service.anonymousUser = null

    /**
     * Initializes this service with supplied user.
     * @param {object} user - the user/trial user as initialized by auth.
     */
    service.init = function (user) {
      // The anonymousId the user has as returned by the backend
      const currentAnonymousId = user.segment_trial_id
      const localAnonymousId = service.getId()
      service.anonymousUser = user

      if (user.status === 'trial' || !currentAnonymousId || currentAnonymousId === 'null') {
        // Process trial/anonymous users or users that for some reason,
        // currently don't have an anonymousId:
        // Always build their anonymousId and store it in localstorage.
        service.setId(service.buildId(user))
      } else if (!localAnonymousId || localAnonymousId !== currentAnonymousId) {
        // Process logged-in users:
        // Update the local anonymousId with the currentAnonymousId retrieved from user object/backend
        // only if there isn't a local id or the local id is different.
        // If the local id is the same as the backend id, do nothing.
        service.setId(currentAnonymousId)
      }
    }

    /**
     * Builds the anonymousId for the supplied user.
     */
    service.buildId = function (user) {
      return `${config.env.shortCode}_${user.id}`
    }

    /**
     * Removes the anonymousId from localstorage.
     */
    service.clearId = function () {
      localStorage.removeItem(LS_ANONYMOUS_KEY)
      return $q.resolve()
    }

    /**
     * Returns a promise of the anonymousId of the current user.
     * This should not be doing any setting of the anonymousId,
     * but its concern is with returning a non-null anonymousId.
     * @return {Promise}
     */
    service.getId = function () {
      const deferred = $q.defer()
      let anonymousId = localStorage.getItem(LS_ANONYMOUS_KEY)

      if (anonymousId && anonymousId !== 'null') {
        // Return the anonymousId if it's valid.
        deferred.resolve(anonymousId)
      } else if (service.anonymousUser) {
        // If the service user has already been initialized,
        // use that user's information when trying to build the id.
        anonymousId = service.buildId(service.anonymousUser)
        deferred.resolve(anonymousId)
      } else {
        // Resolve null since the service is not initialized
        deferred.resolve(null)
      }

      return deferred.promise
    }

    /**
     * Sets the anonymousId of the current user to the supplied value.
     * @param {string} id - the id
     */
    service.setId = function (id) {
      localStorage.setItem(LS_ANONYMOUS_KEY, id)
    }

    /**
     * Updates the user's anonymousId on the backend
     * @return {Promise}
     */
    service.updateBackendId = function () {
      // Avoid circular dependency issue using dynamic injector.
      const auth = $injector.get('auth')
      return service.getId().then(anonymousId => {
        auth.update({ segment_trial_id: anonymousId })
      })
    }
  })
