import angular from 'angular'
import template from './offerCell.html'

export default angular.module('OfferCellComponentModule', []).directive('offerCell', function () {
  return {
    scope: {
      offer: '=',
      promotedMedia: '=',
      location: '@',
    },
    replace: true,
    template,
    controller($scope, config, lang, offerDetailsModalService) {
      Object.assign($scope, { config, lang, offerDetailsModalService })
    },
  }
})
