import angular from 'angular'

export default angular
  .module('subscriptionsServicesModule', [])

  .service('subscriptionsService', function subscriptionsService($q, Restangular, config) {
    const service = this

    service.fetch = function () {
      return config.hasSubscriptions
        ? Restangular.all('store_products').getList({ type: 'subscription' })
        : $q.resolve([])
    }
  })
