import angular from 'angular'
import { order } from '@instacart/enterprise-services'

// cannot lazyload controllers because of nested views that relies on them
import OrderController from './orderController'
import OrderHistoryController from './orderHistoryController'
import OrderEditItemController from './orderEditItemController'
import OrderIcRedirectController from './orderIcRedirectController'
import PurchaseController from './purchaseController'

export default angular
  .module('OrderHistoryComponentModule', [
    OrderController.name,
    OrderHistoryController.name,
    OrderEditItemController.name,
    OrderIcRedirectController.name,
    PurchaseController.name,
  ])
  .config(($stateProvider, langProvider) => {
    // ORDER LIST VIEW
    $stateProvider.state('main.account.orderHistory', {
      url: '/order-history?{selectedRange}&{selectedType}',
      views: {
        'content@main.account': {
          templateUrl: 'orderHistory.html',
          controller: 'OrderHistoryController',
        },
      },
      params: {
        order_cursor: {
          value: '',
        },
        purchase_cursor: {
          value: '',
        },
        nextCursor: {
          value: '',
        },
        selectedRange: {
          value: '30-days',
          squash: true,
        },
        selectedType: null,
      },
      resolve: {
        title(titleService) {
          return titleService.setTitle(langProvider.values.onlineOrderHistory)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.orderHistory,
          },
        ],
      },
    })
    // OneCart order history redirect view
    $stateProvider.state('main.account.orderic', {
      url: '/order/ic/{id}?{add_to_order}',
      views: {
        'content@main.account': {
          template: '',
          controller: 'OrderIcRedirectController',
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.orderHistory,
          },
        ],
      },
    })
    // ORDER DETAILS VIEW
    $stateProvider.state('main.account.orderHistory.order', {
      url: '/order/{id}',
      views: {
        'content@main.account': {
          templateUrl: 'order.html',
          controller: 'OrderController',
          resolve: {
            payment($q, Restangular) {
              const deferred = $q.defer()
              Restangular.one('cart/payment')
                .get()
                .then(
                  payment => {
                    deferred.resolve(payment)
                  },
                  () => {
                    deferred.resolve({}) // Handle 404 when no payment present
                  }
                )
              return deferred.promise
            },
          },
        },
      },
      resolve: {
        title(titleService) {
          return titleService.setTitle(langProvider.values.orderHistory)
        },
        setPageHeaderTitle() {
          return title => (this.data.customPageHeaderTitle = title)
        },
        order(Restangular, $stateParams, oneCartService) {
          return order.api.orders.get($stateParams.id, {
            one_cart_enabled: oneCartService.enabled(),
            user_timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
          })
        },
      },
      params: {
        message: null,
        error: null,
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            state: 'main.account.orderHistory',
            label: langProvider.values.orderHistory,
          },
          {
            label: langProvider.values.orderDetails,
          },
        ],
        customPageHeaderTitle: null,
      },
    })

    // ORDER EDIT ITEMS
    $stateProvider.state('main.account.orderHistory.order.edit', {
      url: '/edit',
      views: {
        'content@main.account': {
          templateUrl: 'orderEditItem.html',
          controller: 'OrderEditItemController',
          resolve: {
            order($rootScope, $stateParams, oneCartService) {
              // If coming back from the Add page or fetch a new order
              return (
                $rootScope.orderEditItemStash?.order ||
                order.api.orders.get($stateParams.id, {
                  with_offer_progress: true,
                  one_cart_enabled: oneCartService.enabled(),
                  user_timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
                })
              )
            },
            savedOrderItemStash($rootScope) {
              return $rootScope.orderEditItemStash
            },
          },
        },
      },
      onExit($rootScope) {
        // When leaving main.account.orderHistory.order.edit remove any info stashed
        // Info gets stashed when going to main.account.orderHistory.order.edit.append route
        // Exit occurs when leaving main.account.orderHistory.order.edit (even leaving .append)
        $rootScope.orderEditItemStash = null
      },
      resolve: {
        title(titleService) {
          return titleService.setTitle(langProvider.values.orderHistory)
        },
        setPageHeaderTitle() {
          return title => (this.data.customPageHeaderTitle = title)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            state: 'main.account.orderHistory',
            label: langProvider.values.orderHistory,
          },
          {
            state: 'main.account.orderHistory.order',
            label: langProvider.values.orderDetails,
          },
          {
            label: langProvider.values.orderEditItems,
          },
        ],
        customPageHeaderTitle: null,
      },
    })

    /**
     * This works by showing fixed content over the UI and silently entering modify mode.
     * The product cells are heavily integrated into the cart, so this was an easy way of getting
     * them to work.
     *
     * - enableAddItemFlow             = Enable this flow
     *
     * Future work:
     * - includeCurrentItemsInAddFlow  = Clear items in modify mode to make it seem only adding
     */
    $stateProvider.state('main.account.orderHistory.order.edit.append', {
      url: '/append?search_term&page',
      views: {
        'fixed-content@': {
          templateUrl: 'orderAddItems.html',
          controller: 'OrderAddItemsController',
          controllerAs: '$ctrl',
        },
      },
      onEnter($rootScope, $state, config) {
        // Get out of here if not coming from the edit page (i.e. no refresh allowed)
        // Get out of here if not enabled
        if (!$rootScope.orderEditItemStash || !config.orderHistoryView.useItemEditAddFlow) {
          $state.go(this.data.vmExtras.PARENT_ROUTE)
        }
      },
      onExit($rootScope, $state, $timeout, cartService) {
        // Cancel modify when exiting irregularly - $timeout ensures $state is the next route
        $timeout(() => {
          if (
            $state.current.name !== this.name && // Has changed route
            !cartService.isCancellingOrderModification && // Not currently in progress of cancelling
            $rootScope.modifyingOrder // Is modifying order still
          ) {
            // We clear items first because there's some kind of cart sync bug when exiting modify
            // from a fixed view where edited items stick around, and we don't want them carrying over!
            // (Probably related to controllers that get unloaded)
            $rootScope.isAppLoading = true
            cartService
              .removeItems(cartService.cart.items)
              .then(() => cartService.cancelOrderModification(this.data.vmExtras.PARENT_ROUTE))
              .finally(() => ($rootScope.isAppLoading = false))
          }
        })
      },
      resolve: {
        /* Load dependencies */
        $lazyLoadControllers(utilService) {
          return utilService.lazyLoadControllers({
            OrderAddOnController: () =>
              import(
                /* webpackChunkName: "orderAddOnController", webpackPrefetch: true */ '../../orderAddOn/orderAddOnController'
              ),
            OrderAddItemsController: () =>
              import(
                /* webpackChunkName: "orderAddItemsController", webpackPrefetch: true */ './orderAddItemsController'
              ),
          })
        },
        $getOrder($rootScope) {
          return (this.data.vmExtras.order = $rootScope.orderEditItemStash?.order)
        },
      },
      data: {
        /* Data captured by the account controller */
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            state: 'main.account.orderHistory',
            label: langProvider.values.orderHistory,
          },
          {
            state: 'main.account.orderHistory.order',
            label: langProvider.values.orderDetails,
          },
          {
            state: 'main.account.orderHistory.order.edit',
            label: langProvider.values.edit,
          },
          {
            label: langProvider.values.addItems,
          },
        ],
        customPageHeaderTitle: null,

        /* Data to merge into controller vm for orderAddItemsController and orderAddOnController */
        vmExtras: {
          isAddOnFullProductCell: true,
          isLoading: false,
          isOrderAddOnInline: true,
          order: null,
          productsPerPage: 12,
          searchCategories: null,
          upsellResults: [],
          PARENT_ROUTE: 'main.account.orderHistory.order.edit',
        },
      },
    })

    // ORDER PURCHASE VIEW ???
    $stateProvider.state('main.account.orderHistory.purchase', {
      url: '/purchase/{id}',
      views: {
        'content@main.account': {
          templateUrl: 'purchase.html',
          controller: 'PurchaseController',
          resolve: {
            purchase(Restangular, $stateParams) {
              return order.api.purchases.get($stateParams.id)
            },
          },
        },
      },
      resolve: {
        title(titleService) {
          return titleService.setTitle(langProvider.values.inStorePurchases)
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            state: 'main.account.orderHistory',
            label: langProvider.values.orderHistory,
          },
          {
            label: langProvider.values.purchaseDetails,
          },
        ],
      },
    })
  })
