import angular from 'angular'
import template from './contactForm.html'

const CONTACT_FORM_ID = 'contact'
const PRIMARY_NUMBER_KEY = 'mobile_phone_number'

class ContactForm {
  constructor($scope, auth, checkoutAnalytics, config, lang, utilService) {
    const localContact = {
      first_name: '',
      last_name: '',
      phone_number: '',
    }

    const shouldUpdateProfile = !config.profile.disabledNameFields

    Object.assign(this, {
      $scope,
      auth,
      checkoutAnalytics,
      config,
      hasErrors: false,
      lang,
      localContact,
      shouldUpdateProfile,
      utilService,
      isLoading: false,
    })
  }

  $onInit() {
    this.$scope.$watch(
      () => this.editMode,
      () => {
        if (this.editMode && this.contact) {
          this.localContact = { ...this.contact }
        }
      }
    )
  }

  /**
   * Returns the contact full name
   *
   * @returns { string }
   */
  get fullName() {
    return `${this.contact.first_name} ${this.contact.last_name}`
  }

  hasRequiredInformation() {
    const { first_name, last_name, phone_number } = this.contact || {}

    return !!(first_name && last_name && phone_number)
  }

  /**
   * Changes the section to edit mode by calling the
   * edit prop function
   *
   * @returns { undefined }
   */
  enterEditMode() {
    const { checkoutAnalytics, contact } = this
    this.edit({ step: CONTACT_FORM_ID })
    checkoutAnalytics.viewContactOptions({ contact })
  }

  /**
   * Handles the contact form submit
   *
   * @returns { undefined }
   */
  handleSubmit() {
    const { checkoutAnalytics, localContact } = this
    const { first_name, last_name, phone_number, address1, postal_code } = localContact

    const contact = {
      delivery_address: {
        first_name,
        last_name,
        address1,
        postal_code,
      },
      primary_number_key: this.contact.primary_number_key || PRIMARY_NUMBER_KEY,
    }

    contact[this.contact.primary_number_key || PRIMARY_NUMBER_KEY] = phone_number

    const logEditContact = success => checkoutAnalytics.editContactOptions({ success })

    if (this.form.$valid) {
      this.hasErrors = false

      if (!this.shouldUpdateProfile) {
        this.onSave({ fetch: false, contact: this.localContact })
        return
      }

      this.isLoading = true

      this.auth
        .updateProfile(contact)
        .then(() => this.onSave({ fetch: true, contact: this.localContact }))
        .then(() => logEditContact(true))
        .catch(error => {
          this.onError({ error })
          logEditContact(false)
        })
        .finally(() => {
          this.isLoading = false
        })
    } else {
      this.hasErrors = true
      logEditContact(false)
    }
  }

  handleBlur() {
    this.hasErrors = !this.form.$valid
  }
}

export default angular.module('ContactFormModule', []).component('contactForm', {
  bindings: {
    address: '<',
    editMode: '<',
    title: '@',
    contact: '<',
    onError: '&',
    onSave: '&',
    edit: '&',
    condensed: '<',
    hideChevron: '<',
  },
  template,
  controller: ContactForm,
})
