import angular from 'angular'

export default angular
  .module('LegacyModalModule', [])
  .service('legacyModalService', function legacyModalService($uibModalStack) {
    const service = this

    service.closeOpenModals = () => {
      if ($uibModalStack.getTop()) {
        $uibModalStack.dismissAll()
      }
    }
  })
