import angular from 'angular'

export default angular
  .module('InstacartCustomerLinkServiceServiceModule', [])
  .service('instacartCustomerLinkService', function instacartCustomerLinkService(Restangular) {
    const service = this

    service.sendLinkCode = () => {
      // POST /users/verify
      return Restangular.one('users/verify').post()
    }

    service.verifyLinkCode = verificationCode => {
      // POST users/verify/confirm
      return Restangular.one('users/verify/confirm').customPOST({ code: verificationCode })
    }

    service.retryLinking = () => {
      return Restangular.one('users/services').post()
    }
  })
