import angular from 'angular'
import carousel from 'angular-carousel'

export default angular
  .module('MobileOnboardingModalControllerModule', ['angular-carousel'])
  .controller(
    'MobileOnboardingModalController',
    function ($scope, $timeout, config, lang, shoppingSelectorService, utilService) {
      $scope.lang = lang
      $scope.slides = config.mobileOnboarding.slides
      $scope.callToActionText = config.mobileOnboarding.callToActionText
      $scope.modalOpen = true
      $scope.utilService = utilService

      $scope.callToAction = function () {
        $scope.modalOpen = false

        $timeout(() => {
          $scope.$close()
        }, 150)
      }
    }
  )
