import React from 'react'
import { CSSObject } from '@emotion/core'
import { LoadingDots } from '@instacart/cocktail'

export interface Props {
  color?: string
  height?: number | string
  inline?: boolean
}

const container = ({ height, inline }: Pick<Props, 'height' | 'inline'>): CSSObject => ({
  alignItems: 'center',
  display: inline ? 'inline-flex' : 'flex',
  height: height ?? '100%',
  justifyContent: 'center',
  minHeight: '1.5em',
  opacity: '0.5',
})

const LazyLoadingIndicator = ({ color, height, inline }: Props) => (
  <LoadingDots css={container({ height, inline })} dotColor={color ?? 'currentColor'} />
)

export default LazyLoadingIndicator
