import angular from 'angular'
import template from './subscriptionSelect.html'

export default angular.module('subscriptionSelect', []).component('subscriptionSelect', {
  bindings: {},
  template,
  controller: [
    'analytics',
    'auth',
    'cartService',
    'config',
    'lang',
    'subscriptionsService',
    function (analytics, auth, cartService, config, lang, subscriptionsService) {
      const vm = this

      vm.$onInit = () => {
        Object.assign(vm, {
          analytics,
          auth,
          cartService,
          config,
          lang,
          subscriptionsService,
        })

        vm.subscriptionsService
          .fetch()
          .then(subscriptions => {
            vm.subscriptions = subscriptions.plain()
          })
          .catch(err => {
            throw new Error(`Ooops, something went wrong, ${err}`)
          })
      }

      vm.onSubscriptionSelected = ({ selectedSubscription, oldSubscription }) => {
        if (selectedSubscription) {
          vm.cartService
            .removeSubscriptions()
            .then(() => vm.cartService.addItem(selectedSubscription, undefined, undefined, false))
            .then(() => vm.cartService.validateCart())
            .then(() => vm.analytics.logEvent('checkout payment subscribe button', 'click'))
        } else if (oldSubscription) {
          vm.cartService
            .removeSubscriptions()
            .then(() => vm.cartService.validateCart())
            .then(() =>
              vm.analytics.logEvent('checkout payment remove subscription button', 'click')
            )
        }
      }
    },
  ],
})
