import angular from 'angular'
import { EXPIRATION_DATE_FORMAT } from '@app/angular/constants/express'
import template from './secondStep.html'

class ExpressCancelFirstStep {
  constructor($filter, config, express, lang) {
    Object.assign(this, {
      $filter,
      config,
      express,
      lang,
    })
  }

  $onInit() {
    this.subTitle = this.getSubtitle()
    this.bottomText = this.getBottomText()
    this.planLabel = this.getPlanPriceText()
    this.membershipButtonLabel = this.getMembershipButtonLabel()
  }

  getMembershipButtonLabel() {
    const { icExpress } = this.config

    return this.lang.template(icExpress.cancellation.secondStep.buttonLabel, {
      price: this.getPlanPriceText(),
      express: icExpress.shortName,
    })
  }

  getSubtitle() {
    return this.lang.template(this.config.icExpress.cancellation.secondStep.subTitle, {
      expressName: this.config.icExpress.name,
      chargeDate: this.express.getMembershipEndDate().format(EXPIRATION_DATE_FORMAT),
    })
  }

  getRenewReminderText() {
    const { membership, trial } = this.lang.values.icExpress
    const plan = this.express.isTrial() ? trial : membership

    return this.lang.templateAsString(this.config.icExpress.cancellation.renewReminder, {
      plan,
    })
  }

  getBottomText() {
    const buttonLabel = this.lang.templateAsString(
      this.config.icExpress.cancellation.secondStep.buttonLabel,
      {
        price: this.getPlanPriceText(),
      }
    )

    return this.lang.templateAsString(this.config.icExpress.cancellation.secondStep.bottomText, {
      buttonLabel,
      chargeDate: this.express.getMembershipEndDate().format(EXPIRATION_DATE_FORMAT),
      planPrice: this.getPlanPriceText(),
    })
  }

  getPlanPriceText() {
    return this.$filter('currency')(this.config.icExpress.plans.monthly.price)
  }
}

export default angular
  .module('IcExpressCancelSecondStepComponentModule', [])
  .component('expressCancelSecondStep', {
    template,
    bindings: {
      useIcTheme: '<',
      onContinueClick: '&',
      onSendReminderClick: '&',
      onUpdateMembershipClick: '&',
      termsOfService: '<',
    },
    controller: ExpressCancelFirstStep,
  })
