import angular from 'angular'
import tokens from '@app/angular/services/modules/tokens'
import { shoppingList as shoppingListService } from '@instacart/enterprise-services'
import { utils as timeslotUtils } from '@instacart/enterprise-timeslots'
import { DEFAULT_TIMESLOT_DATE_FORMAT } from '@app/angular/constants/timeslots'
import fulfillmentListTemplate from './fulfillmentList.html'
import desktopTemplate from './fulfillmentList.desktop.html'
import mobileTemplate from './fulfillmentList.mobile.html'

export default angular
  .module('FulfillmentListComponentModule', [])
  .component('fulfillmentList', {
    transclude: true,
    bindings: {
      checkoutCartContinue: '&',
    },
    template: fulfillmentListTemplate,
    controller(
      $filter,
      $scope,
      $state,
      analytics,
      auth,
      cartService,
      checkoutService,
      checkoutAnalytics,
      config,
      express,
      lang,
      loyaltyService,
      mobileService,
      modalOptions,
      reactCartLoyaltyBalanceDefaultProps,
      reservationService,
      shoppingContextService,
      shoppingSelectorService,
      userService,
      utilService
    ) {
      const {
        intent,
        user: { context, store },
      } = auth
      const { shoppingSelector } = config
      const { delivery, pickup } = shoppingSelector
      const { cart } = cartService
      const { checkoutCartContinue } = $scope.$ctrl
      const scheduleModalConfigFactory = () => ({
        showBackButton: false,
        showTimeslotSelectLaterLink: false,
      })
      const setTotalRewards = () => {
        $scope.rewards = loyaltyService.getVisibleRewards(
          cartService.cart?.cart_totals?.[cartService.getCurrentCartType()]?.reward_discounts
        )
      }

      Object.assign(
        $scope,
        {
          auth,
          cartService,
          checkoutCartContinue,
          config,
          reservationService,
          intent,
          lang,
          loyaltyService,
        },
        {
          mobile: mobileService,
          userStore: store || {},
          currencyFilter: $filter('currency'),
          getMinCartSpend: shoppingListService.commonUtils.getMinimumCartSpend,
          deliveryAddress: { postal_code: context.deliveryPostalCode },
          shoppingIntentTitle:
            shoppingContextService.getShoppingModeTitle() || lang.values.pleaseSelect,
          showContextChangeBtn: delivery.exists || pickup.exists,
          showOrderOnlineButton: cartService.canOrderOnline(),
          openShoppingSummary: shoppingSelectorService.open,
          orderNotesEditing: false,
          loyaltyReactProps: {
            ...reactCartLoyaltyBalanceDefaultProps,
            styles: { value: { color: tokens.color.brand.primary.dark } },
          },
          hasCartItems: () => cart.items.length > 0,
          openPromoCodeModal: () =>
            utilService.openPromoCodeModal(cartService.sync, undefined, true),
          openZipCodeModal: () =>
            $scope.openShoppingSummary('storeSelector', null, {
              showBackButton: false,
            }),
          openScheduleModal: () =>
            $scope.openShoppingSummary('timeslot', null, scheduleModalConfigFactory()),

          saveOrderNotes: () => {
            let success = true
            try {
              cartService.sync()
              analytics.logEvent('cart save comment update', 'update')
            } catch {
              success = false
            }

            checkoutAnalytics.modifyNote({ success, note: cartService.cart?.comment })
          },
          toggleOrderNotes: () => {
            if (!$scope.orderNotesEditing) {
              $scope.orderNotesEditing = true
            } else {
              $scope.saveOrderNotes()
              $scope.orderNotesEditing = false
            }
          },
          handleChangeStoreClick: () => {
            modalOptions.reset()
            modalOptions.setOptions({ showBackButton: false })
            $scope.openShoppingSummary('storeSelector')
          },
          showLoyaltyCartPagePlacement: () =>
            loyaltyService.showLoyaltyPlacement() && config.loyalty.enableLoyaltyOptIn.cartPage,
          showLoyaltyCardBalance: () =>
            userService.loyaltyCards.length > 0 &&
            // eslint-disable-next-line no-restricted-globals
            !isNaN(userService.getLoyaltyCard().points_balance) &&
            config.hasLoyalty &&
            config.showLoyaltyBalanceInCart,
        }
      )

      checkoutService.getSelectedDeliveryAddress(false).then(address => {
        $scope.deliveryAddress = address || $scope.deliveryAddress
      })

      if (config.rewards.visible.length) {
        $scope.$watch(() => cartService.syncing, setTotalRewards)
        setTotalRewards()
      }
    },
  })
  .filter('offersToPromoCodes', function () {
    return offers =>
      offers &&
      offers.filter(
        offer => offer.offer.promo_code !== null && offer.offer.promo_code !== undefined
      )
  })
  .filter('reservationScheduleToString', (config, reservationService) => {
    return reservation => {
      return timeslotUtils.getMainLabel(reservation, {
        format: reservationService.reservation
          ? config.apiDateFormat
          : DEFAULT_TIMESLOT_DATE_FORMAT,
      })
    }
  })
  .run($templateCache => {
    $templateCache.put('fulfillmentList.desktop.html', desktopTemplate)
    $templateCache.put('fulfillmentList.mobile.html', mobileTemplate)
  })
