import _ from 'lodash'
import angular from 'angular'
import { imageTransform } from '@instacart/enterprise-services'
import CellServiceModule from './cellService'

export default angular
  .module('CellImageModule', [CellServiceModule.name])
  .directive('cellImage', function (mobileService, utilService) {
    return {
      restrict: 'A',
      scope: {
        promotedMedia: '=',
        defaultImage: '=',
        useImageTag: '<',
        lazyLoadImage: '<',
        fallbackImage: '@',
      },
      link(scope, element) {
        // Check if browser supports observing
        const canObserve = () => utilService.hasIntersectionObserver() && scope.lazyLoadImage
        // Watch images
        scope.$watchGroup(['promotedMedia', 'defaultImage'], () => {
          let image = utilService.escapeImageUrl(scope.defaultImage)
          let promotedImage = false
          let altText = ''

          // Select a promoted media image, if one is appropriate

          if (scope.promotedMedia) {
            if (scope.promotedMedia.mobile && mobileService.isMobile) {
              image = utilService.escapeImageUrl(
                imageTransform(scope.promotedMedia.mobile.image_url)
              )
              altText = scope.promotedMedia.mobile.alt_text
              promotedImage = true
            } else if (
              scope.promotedMedia.desktop &&
              (!mobileService.isMobile || scope.promotedMedia.desktop.span <= 2)
            ) {
              image = utilService.escapeImageUrl(
                imageTransform(scope.promotedMedia.desktop.image_url)
              )
              altText = scope.promotedMedia.desktop.alt_text
              promotedImage = true
            }
          }

          // We have to check for the undefined string as utilService.escapeImageUrl() always returns a string
          if (image === 'undefined' && !_.isNil(scope.fallbackImage)) {
            image = utilService.loadStaticImage(scope.fallbackImage)
          }

          // Mark image as promoted
          element.parent().toggleClass('promoted', promotedImage)

          // Fade in image
          if (image) {
            element.removeClass('show')
            const observableElement = element

            // Conditionally check to see if `use-image-tag` attr is set to true
            // If so, append an image element as a child in order to implement alt text and be semantically correct for accessibility
            // Otherwise, maintain the "old" way of showing images as `background-image`. This is a TODO to eventually convert
            // All images to follow this pattern and deprecating the using of `background-image`
            if (scope.useImageTag) {
              const imageElement = angular.element('<img>')
              imageElement[0].dataset.src = image
              imageElement[0].alt = altText || ''

              if (element[0].children.length > 0) {
                element[0].innerHTML = ''
              }

              element[0].appendChild(imageElement[0])
            } else {
              observableElement[0].dataset.src = image
            }

            const loadImage = (loadedImageElement, loadImage) => {
              const img = new Image()
              img.onload = function () {
                loadedImageElement.addClass('show')
              }

              img.src = loadImage

              if (scope.useImageTag) {
                loadedImageElement[0].querySelector('img').src = loadImage
              } else {
                loadedImageElement[0].style.backgroundImage = `url(${utilService.escapeImageUrl(
                  loadImage,
                  true
                )})`
              }
            }

            // Lazy load images if supported
            if (canObserve()) {
              const observerOptions = {
                root: null,
                rootMargin: '0px',
                threshold: 0.1, // percentage of image that needs to be in view
              }

              const handleIntersection = (entries, self) => {
                entries.forEach(entry => {
                  if (entry.isIntersecting) {
                    loadImage(observableElement, image)
                    self.unobserve(entry.target)
                  }
                })
              }

              // The observer for the images on the page
              const observer = new IntersectionObserver(handleIntersection, observerOptions)
              observer.observe(observableElement[0])
            } else {
              loadImage(observableElement, image)
            }
          } else {
            element[0].style.backgroundImage = 'none'
          }
        })
      },
    }
  })
