import React, { memo } from 'react'
import PropTypes from 'prop-types'
import { createStyles, Text, utils } from '@instacart/cocktail'

const useStyles = createStyles(
  tokens => ({
    root: {
      textAlign: 'center',
      width: '100%',
    },
    title: {
      fontSize: tokens.size.font.h2,
      [utils.mediaQuery.minWidth(tokens.breakpoint.small)]: {
        fontSize: tokens.size.font.h1,
      },
    },
    subtitle: {
      fontSize: tokens.size.font.regular,
      display: 'inline-block',
      maxWidth: '850px',
      [utils.mediaQuery.minWidth(tokens.breakpoint.small)]: {
        fontSize: tokens.size.font.large,
      },
    },
  }),
  { displayName: 'SubscriptionCheckoutMarketing' }
)

const defaultLang = {
  title: 'Get unlimited shopping for only $49/yr.',
  subtitle:
    'With Fresh Grocery Express, you can avoid the crowded aisles, skip the long checkout lines, and save time and energy for everything else in your life.',
}

const Header = props => {
  const styles = useStyles(props)
  const { i18n, className } = props

  const lang = { ...defaultLang, ...i18n }
  return (
    <div css={styles.root} className={className}>
      <Text variant="h2" color="textPrimary" css={styles.title}>
        {lang.title}
      </Text>
      <Text variant="h3" color="brandSecondary" css={styles.subtitle}>
        {lang.subtitle}
      </Text>
    </div>
  )
}

Header.propTypes = {
  /** Custom style class name for the containing element */
  className: PropTypes.string,
  /** Custom language strings */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
}

export default memo(Header)
