import angular from 'angular'

export default angular
  .module('promoCodeValidationServicesModule', [])
  .service('promoCodeValidationService', function promoCodeValidationService(Restangular) {
    const service = this

    service.checkIfValidPromoCode = promo => {
      // Use the provided promoCode to make an API call trying to unlock an offer.
      if (promo.promo_code.length) {
        return Restangular.one('cart').customPOST(promo, 'promo_codes')
      }
    }
  })
