/*
Purpose of this function is to add support for specific cookies which are critical for partners due to 3rd party integration
The way how this works is it allows persisting cookies in localStorage when cookies are not supported
This is an issue on iOS Capacitor, when you serve files from localhost (in-app browser works just fine) https://github.com/ionic-team/capacitor/issues/1373
Please make sure to check TODO comments, there is a lot to do still if we need full cookies support. On Nov 18, 21 we only focus on addressing Adobe Target issues for Wegmans
Note, that we hope to migrate to live WebView in near future and because of that we hope we don't need to maintain this for long
*/
import isCapacitor from '@app/utils/isCapacitor'
import isMobile from 'ismobilejs'

// Leave empty to keep all cookies, note that this was not tested and will likely not work out of the box for you. Keep in mind comment about domain check below
export const whitelistedCookies = [
  'at_check',
  'mbox',
  's_vi',
  's_cc',
  's_ips',
  's_loginSuccess',
  's_ppv',
  's_tp',
  's_sq',
  'AMCVS_68B620B35350F1650A490D45%40AdobeOrg',
  'AMCV_68B620B35350F1650A490D45%40AdobeOrg',
]

export function fixCapacitorCookies() {
  if (!isCapacitor() || !isMobile.apple.device) {
    return
  }

  /* TODO:
   - note, if any changes to the structure are done, consider if you want to copy cookies from existing storage to migrate them
   - need to handle session expiration
   - path match
   - session cookies -> sessionStorage? likely no because reading, & sync becomes harder
    - Maybe some identifier can be stored in sessionStorage and if it is gone, all keys prefixed with the value should be removed
    */
  const testCookie = 'some_random__and__unique_cookie=1'
  document.cookie = testCookie
  const cookieSeparator = '; '
  // TODO provide a way to clear all cookies so state is all in sync, as currently due to memory optimizations you need to clear storage, then refresh the page
  const isCookieSupported = !!document.cookie.split(cookieSeparator).find(x => x === testCookie)
  if (isCookieSupported) {
    document.cookie = `${testCookie}=; expires=Fri, 27 Jul 2001 02:47:11 UTC`
    printWarning('cookies are now supported, this code may be not necessary anymore')
  } else {
    printWarning('cookies are not supported, creating a fallback for Adobe Target')
    const storageKey = '_local-cookies_'
    const memoryStorage = JSON.parse(localStorage.getItem(storageKey) || '{}')
    // reduce number of times we need to produce a document.cookie string
    let isStale = true
    let cache = ''

    Object.defineProperty(document, 'cookie', {
      get() {
        if (isStale) {
          cache = Object.entries(memoryStorage)
            .map(([key, value]) => `${key}=${value}`)
            .join(cookieSeparator)
          isStale = false
        }
        return cache
      },
      set(x) {
        const parts = `${x}`.split(cookieSeparator)
        // const domain = parts.find(x => x.startsWith('domain='))
        const entry = parts[0]
        const [key, value] = entry.split('=')

        if (
          typeof x !== 'string' ||
          // TODO some domains are restricted and should not support setting on root level (ex: glitch.me, github.io, ...)
          //  also instead of direct domain check we may check for top level?
          // for now commented out since we limited check to whitelisted cookies
          // (domain && domain !== `domain=${location.hostname}`) ||
          !key ||
          (whitelistedCookies.length && !whitelistedCookies.includes(key))
        ) {
          return 'not=successful'
        }

        if (value === '') {
          delete memoryStorage[key]
        } else {
          memoryStorage[key] = value
        }
        isStale = true
        localStorage.setItem(storageKey, JSON.stringify(memoryStorage))
        return entry
      },
    })
  }

  function printWarning(msg: string) {
    if (typeof jest === 'undefined') {
      console.warn(msg)
    }
  }
}
