import _ from 'lodash'
import angular from 'angular'
import * as catalogSelectors from '@app/services/catalog/selectors'
import { actions } from '@app/services/catalog/slice'
import * as thunks from '@app/services/catalog/thunks'
import { store as reduxStore } from '@app/services/store'

export default angular
  .module('catalogCategoriesServiceComponentModule', [])
  .service(
    'catalogCategoriesService',
    function catalogCategoriesService($filter, $ngRedux, $q, eComLightService, lang, tagsService) {
      const service = this
      let cachePromise

      service.tags = []

      const mapStateToService = state => {
        const categories = catalogSelectors.categoriesSelector(state)

        // Fetch a list of all unique tags present in category structure
        categories.forEach(category => {
          if (category && category.tags) {
            category.tags.forEach(tag => {
              if (service.tags.indexOf(tag) === -1) {
                service.tags.push(tag)
              }
            })
          }
        })

        return { categories }
      }

      $ngRedux.connect(mapStateToService, {
        ...actions,
      })(service)

      service.fetch = function (refresh) {
        const deferred = $q.defer()

        if (!refresh && service.categories) {
          deferred.resolve(service.categories)
          return deferred.promise
        }

        reduxStore
          .dispatch(thunks.fetchCategories())
          .then(() => deferred.resolve(service.categories))
          .then(() => {
            if (eComLightService.eComLightCategory) {
              const eComLightCategoryObj = service.categoryFromId(
                eComLightService.eComLightCategory
              )
              eComLightService.setEComLightCategories(eComLightCategoryObj)
            }
          })
          .finally(() => {
            cachePromise = null
          })

        return deferred.promise
      }

      // If multiple fetch calls are made from different components, intercept them
      // and send along the result of the promise already started.
      service.fetchCategories = function (refresh) {
        if (!refresh && cachePromise) {
          return cachePromise
        } else {
          cachePromise = service.fetch(refresh)
          return cachePromise
        }
      }

      // Find / fetch all categories without a parent_id or featured department placements
      // and construct a rootCategory object array that can be consumed by the rest of the
      // frontend platform
      service.rootCategories = function (featuredDepartmentPlacements) {
        const findRootCategories = () => {
          if (service.categories) {
            return _.filter(service.categories, category => {
              return !category.parent_id
            })
          } else {
            service.fetchCategories().then(result => {
              return _.filter(result, category => {
                return !category.parent_id
              })
            })
          }
        }

        const findFeaturedDepartments = () => {
          if (service.categories && featuredDepartmentPlacements) {
            return _.filter(featuredDepartmentPlacements, featured_department => {
              return featured_department.id
            })
          }
        }

        // Build the root categories from featured departments + roots
        const buildRoot = () => {
          const rootCategories = findRootCategories()
          const rootFeatured = findFeaturedDepartments() || []

          // Provide the keys necessary for featured department placements to
          // function identically to traditional categories
          _.each(rootFeatured, featured => {
            featured.state = 'main.shop.mlp'
            featured.params = { mlp_id: featured.id }
            featured.images = { tile: { medium: featured.media.image || '' } }
            const slug = $filter('slugify')(featured.name)
            featured.link = `shop/featured/${featured.id}/${slug}`
            featured.sref = `main.shop.mlp({mlp_id: ${featured.id}, mlp_slug: ${JSON.stringify(
              slug
            )}, tags: null, page: null, brand_name: null, sort: null})`
            featured.children = [
              // Omit until we can test actual fetch performance, will be added in a future commit
              // {
              //   name: 'child',
              //   state: 'main.shop.collections',
              //   sref: 'main.shop.collections',
              // },
            ]
          })

          return rootFeatured.concat(rootCategories)
        }

        if (service.categories) {
          return buildRoot()
        } else {
          service.fetchCategories().then(() => {
            return buildRoot()
          })
        }
      }

      // Returns a list of root categories that should be visible, as permitted by eCom Light
      // rules. This can't be applied at load as we need the full root category hierarchy
      // elsewhere.
      service.visibleRootCategories = featuredDepartmentPlacements => {
        return service.rootCategories(featuredDepartmentPlacements).filter(category => {
          return eComLightService.isNonEComLightCategory(category)
        })
      }

      service.parents = function (category) {
        const parents = []

        if (category) {
          let child = category
          let parent

          while ((parent = _.find(service.categories, { id: child.parent_id }))) {
            parents.unshift(parent)
            child = parent
          }
        }

        return parents
      }

      service.siblings = function (category) {
        let siblings = []

        if (category) {
          siblings = _.filter(service.categories, c => {
            return c.parent_id === category.parent_id
          })
        }

        return siblings
      }

      service.children = function (category) {
        let children = []

        if (category) {
          children = _.filter(service.categories, c => {
            return c.parent_id === category.id
          })
        }

        return children
      }

      service.categoryFromId = function (id) {
        if (id) {
          return _.find(service.categories, { id: id })
        } else {
          return {
            level: null,
            tags: service.tags,
            children: service.rootCategories(),
          }
        }
      }

      /**
       * Creates and returns an object containing a list of ordered categories
       * in the sequence of rootCategories and categorized products with their respective categories.
       * @param  { array } products
       *
       * @returns { Promise<object> } - orderedCategories, categorizedProducts
       *
       */
      service.getOrderedCategoriesAndProductsWithCategories = function (products) {
        return service.fetchCategories().then(() => {
          const rootCategories = service.rootCategories().map(category => category.name)
          rootCategories.unshift(lang.values.uncategorizedCategory)
          const sortedByStockLevelItems = _.sortBy(
            products,
            item => item.store_product?.availability?.stock_level
          )
          const categorizedProducts = _.groupBy(sortedByStockLevelItems, item => {
            if (item.store_product.categories && item.store_product.categories.length > 0) {
              return item.store_product.categories[0].name
            } else return lang.values.uncategorizedCategory
          })
          const productCategories = Object.keys(categorizedProducts)

          // Sorted categorized product categories by order of root categories
          const orderedCategories = rootCategories.filter(
            rootCategory =>
              productCategories.findIndex(productCategory => productCategory === rootCategory) > -1
          )
          return { orderedCategories, categorizedProducts }
        })
      }
    }
  )
