import angular from 'angular'

export default angular.module('RecipeMetaModule', []).component('recipeMeta', {
  bindings: {
    prepInfo: '<',
    isSaved: '=?',
    recipe: '<?',
    savedCount: '<?',
  },
  controller($rootScope, config, lang, mobileService, savedRecipesService) {
    const vm = this
    vm.config = config
    vm.lang = lang
    vm.isMobile = mobileService.isMobile

    vm.toggleSaveRecipe = () => {
      savedRecipesService
        .requireLogin()
        .then(() => {
          if (vm.isSaved) {
            savedRecipesService.removeSavedRecipe(vm.recipe.id, vm.recipe).then(() => {
              vm.isSaved = false
              // eslint-disable-next-line no-plusplus
              vm.savedCount--
            })
          } else {
            savedRecipesService.saveRecipe(vm.recipe.id, vm.recipe).then(() => {
              vm.showSavePrompt = false
              vm.isSaved = true
              // eslint-disable-next-line no-plusplus
              vm.savedCount++
            })
          }
        })
        .catch(() => null)
    }

    if (config.enableSavedRecipes && !config.disableRecipes) {
      // Could not keep this DRY from recipeDetailsController due to Angular scoping, repeating this was a better option
      $rootScope.$on('cartService', (event, action) => {
        if (action === 'add') {
          vm.showSavePrompt = true
        }
      })
    }
  },
  template: `
      <section class="recipe-meta">
        <ul>
          <li>
            <span ng-if="::!$ctrl.isMobile" class="recipe-meta-icon icon-clock" aria-hidden="true"></span>
            <span class="recipe-meta-name">Ready In<span ng-if="::!$ctrl.isMobile">:</span></span>
            <span class="recipe-meta-value">
              <time datetime="{{::$ctrl.prepInfo.total_time | secondsToHoursMins: ({ toDateTime: true })}}">
                {{::$ctrl.prepInfo.total_time | secondsToHoursMins: ({ shortenWords: $ctrl.isMobile })}}
              </time>
            </span>
          </li>
          <li>
            <span ng-if="::!$ctrl.isMobile" class="recipe-meta-icon icon-apron" aria-hidden="true"></span>
            <span class="recipe-meta-name">Prep<span ng-if="::!$ctrl.isMobile">:</span></span>
            <span class="recipe-meta-value">
              <time datetime="{{::$ctrl.prepInfo.preparation_time | secondsToHoursMins: ({ toDateTime: true })}}">
                {{::$ctrl.prepInfo.preparation_time | secondsToHoursMins: ({ shortenWords: $ctrl.isMobile })}}
              </time>
            </span>
          </li>
          <li ng-if="::$ctrl.prepInfo.cook_time > 0">
            <span ng-if="::!$ctrl.isMobile" class="recipe-meta-icon icon-pot" aria-hidden="true"></span>
            <span class="recipe-meta-name">Cook<span ng-if="::!$ctrl.isMobile">:</span></span>
            <span class="recipe-meta-value">
              <time datetime="{{::$ctrl.prepInfo.cook_time | secondsToHoursMins: ({ toDateTime: true })}}">
                {{::$ctrl.prepInfo.cook_time | secondsToHoursMins: ({ shortenWords: $ctrl.isMobile })}}
              </time>
            </span>
          </li>
          <li ng-if="::!$ctrl.isMobile">
            <span class="recipe-meta-icon icon-servings" aria-hidden="true"></span>
            <span class="recipe-meta-name">Servings: </span>
            <span class="recipe-meta-value">
              {{::$ctrl.prepInfo.serving_size}}
            </span>
          </li>
          <li ng-if="::$ctrl.isMobile && $ctrl.config.enableSavedRecipes && !$ctrl.config.disableRecipes" class="recipe-meta-save">
            <span class="recipe-meta-save-button">
              <button ng-click="$ctrl.toggleSaveRecipe()" class="button small hollow icon-{{::$ctrl.config.savedRecipesSaveIcon}} save-recipe-button" ng-class="{'saved': $ctrl.isSaved}">
                <span ng-if="$ctrl.isSaved" class="badge icon-checkmark" aria-hidden="true"></span>
                {{$ctrl.savedCount === 0 ? $ctrl.lang.values.savedRecipes.saveRecipe : $ctrl.lang.values.savedRecipes.savedBy + ' ' + $ctrl.savedCount }}
              </button>
            </span>
            <span class="save-prompt" ng-if="!$ctrl.isSaved && $ctrl.showSavePrompt">
              <span role="button" class="icon-delete" ng-click="$ctrl.showSavePrompt = false" aria-hidden="true"></span>
              {{::$ctrl.lang.values.savedRecipes.promptPrimary}}
            </span>
          </li>
        </ul>
      </section>
    `,
})
