import angular from 'angular'
import AccountMenuServiceModule from './accountMenuService'

export default angular
  .module('AccountNavComponentModule', [AccountMenuServiceModule.name])
  .config($stateProvider => {
    $stateProvider.state('main.account', {
      url: '/account',
      abstract: false,
      data: {
        requireRegistered: true,
      },
      views: {
        'content@': {
          template: require('./accountNav.html'),
          controller: 'AccountNavController',
        },
      },
      resolve: {
        loadCtrl: utilService => {
          return utilService.lazyLoadControllers({
            AccountNavController: () =>
              import(
                /* webpackChunkName: "accountNavController", webpackPrefetch: true */ './accountNavController'
              ),
          })
        },
      },
    })
  })
