import angular from 'angular'

export default angular
  .module('ProfileComponentModule', [])
  .config(($stateProvider, configConstants, langProvider) => {
    $stateProvider.state('main.account.profile', {
      url: '/profile',
      views: {
        'content@main.account': configConstants.gigya.enableProfileEdit
          ? {
              template: require('@app/angular/optionalComponents/gigya/profileGigya.html'),
              controller: 'ProfileGigyaController',
            }
          : {
              template: require('./profile.react.html'),
              controller: 'ProfileReactController',
            },
      },
      resolve: {
        loadCtrl: $ocLazyLoad => {
          if (configConstants.gigya.enableProfileEdit) {
            return import(
              /* webpackChunkName: "profileGigyaController", webpackPrefetch: true */ '@app/angular/optionalComponents/gigya/profileGigyaController'
            )
              .then(mod => $ocLazyLoad.load(mod.default))
              .catch(err => {
                throw new Error(`Ooops, something went wrong, ${err}`)
              })
          }

          return import(
            /* webpackChunkName: "profileReactController", webpackPrefetch: true */ './profileController.react'
          )
            .then(mod => $ocLazyLoad.load(mod.default))
            .catch(err => {
              throw new Error(`Ooops, something went wrong, ${err}`)
            })
        },
        title(titleService) {
          return titleService.setTitle(langProvider.values.profile)
        },
        loyaltyCard(config, userService) {
          if (config.profile.showLoyaltyReadOnly && !userService.loyaltyCards.length) {
            return userService.fetchLoyaltyCards()
          }
        },
      },
      data: {
        breadcrumbs: [
          {
            label: langProvider.values.accountDetails,
          },
          {
            label: langProvider.values.profile,
          },
        ],
      },
    })
  })
