import { config as configService } from '@instacart/enterprise-services'
import { getCurrentBannerConfig } from '@app/utils/banners'

type SupportedModules = 'waysToShop'

export const isModuleEnabled = (module: SupportedModules, bannerKey?: string): boolean => {
  const config = configService.selectors.getConfig()
  return (
    config[module]?.enabled ||
    Boolean(
      bannerKey && config.multipleBanners?.enableModuleByBannerKey[module]?.includes(bannerKey)
    )
  )
}

export const isWaysToShopEnabled = (): boolean => {
  return isModuleEnabled('waysToShop', getCurrentBannerConfig()?.bannerExtId)
}
