import angular from 'angular'
import template from './addToList.html'

export default angular.module('AddToListComponentModule', []).component('addToList', {
  bindings: {
    products: '<',
    toggleStyle: '@?',
    configuration: '<?', // the configuration the user has selected in the PDP
    isConfigurationInvalid: '<?', // if the configuration is valid (has all required fields)
    popoverButtonExtraClass: '@?',
    isExternalRoutesDisabled: '<',
  },
  template,
  replace: true,
  controller($element, $timeout, analytics, auth, lang, list, mobileService) {
    this.isMobile = mobileService.isMobile
    this.isTrialUser = auth.user.status !== 'trial'
    this.lang = lang
    this.lists = list.lists
    this.toggleStyle = this.toggleStyle || 'button'
    this.popoverButtonExtraClass = this.popoverButtonExtraClass || ''

    this.toggled = open => {
      if (!open) {
        // return focus to toggle button once modal is closed
        $element[0].querySelector('.add-to-list-button').focus()
      }
    }

    this.close = function () {
      this.dropdownIsOpen = false
    }

    this.productsAreIn = someList => list.itemsInList(this.products, someList, this.configuration)

    this.productsAreInOneList = () => list.itemsInOneList(this.products, this.configuration)

    this.addOrRemoveItemsFrom = targetList => {
      if (this.products.length > 0) {
        if (this.productsAreIn(targetList)) {
          // Item(s) is/are IN the list, remove it/them
          list.removeItems(this.products, targetList, this.configuration)
        } else {
          // Item(s) is/are NOT in the list, add it/them
          list.addItems(this.products, targetList, this.configuration)
          const payload = {
            product_id: this.products[0].id,
            list_name: targetList.name,
            source: this.toggleStyle === 'icon' ? 'productTile' : 'productDetail',
          }
          analytics.logSegmentEvent('store.save_for_later', payload)
        }
      }
    }

    this.createNewList = () => {
      this.isCreatingNewList = true

      list.create(this.newListName, this.products, this.configuration).then(() => {
        // Reset new list name input and isCreatingNewList flag
        delete this.newListName
        delete this.isCreatingNewList
      })
    }
  },
})
